import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  updateUser,
  createUser,
} from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import { USER_TYPES } from "~/shared/utils/user";
import { isStringEmpty } from "~/shared/utils/strings";
import { isDataValid } from "~/shared/utils/validations";
import { openUserDetails, openAdminUsers } from "~/shared/routes/navigation";
import { SwipeTabs } from "~/shared/components/molecules";
import { Footer } from "./styles";

// TABS
import InformationTab from "./components/InformationTab";
import GroupsTab from "./components/GroupsTab";
import { useTranslation } from "~/shared/hooks/translation";

function UserForm({ user }) {
  const dispatch = useDispatch();
  const { isUpdating, isCreating } = useSelector(
    state => state.adminUsers.list
  );
  const { translate } = useTranslation();

  // STATES
  const editMode = isDataValid(user);
  const [infoTabError, setInfoTabError] = React.useState(false);
  const [formData, setFormData] = React.useState({});
  const [showSaveModal, setShowSaveModal] = React.useState(false);
  const [selectedPartners, setSelectedPartners] = React.useState([]);
  const [groupIds, setGroupIds] = React.useState([]);
  const [accessType, setAccessType] = React.useState(
    editMode ? user.access_type : USER_TYPES.COMMOM_USER.value
  );
  const [sendEmail, setSendEmail] = React.useState(false);

  // TABS
  const tabs = [
    { id: 1, name: translate("common.informations"), error: infoTabError },
    { id: 2, name: translate("common.groups") },
  ];

  // FUNCTIONS
  React.useEffect(() => {
    if (editMode) {
      const { groups, partners_ids } = user;

      if (partners_ids.length) setSelectedPartners(partners_ids);

      if (groups.length) {
        const ids = groups.map(group => group.id);
        setGroupIds(ids);
      }
    }
  }, [editMode, user]);

  function handleSubmit() {
    const { password, password_confirmation } = formData;

    const hasPassword = !isStringEmpty(password);

    const userData = {
      ...formData,
      password: hasPassword ? password : null,
      password_confirmation: hasPassword ? password_confirmation : null,
      groupIds,
      access_type: accessType,
      send_email: sendEmail,
      partners_ids: selectedPartners,
    };

    if (editMode) {
      dispatch(updateUser(userData, user.id));
    } else {
      dispatch(createUser(userData));
    }
  }

  const id = editMode ? user.id : null;
  const name = editMode ? user.name : null;

  React.useMemo(() => {
    if (isUpdating === "FINISH") openUserDetails(id, name);

    if (isCreating === "FINISH") openAdminUsers();
  }, [id, isCreating, isUpdating, name]);

  return (
    <>
      <SwipeTabs size="small" alignment="left" tabs={tabs}>
        <InformationTab
          user={user}
          editMode={editMode}
          formId="user-form-id"
          onSubmit={data => {
            setShowSaveModal(true);
            setFormData(data);
          }}
          onFailed={() => {
            setInfoTabError(true);
          }}
          setSendEmail={setSendEmail}
          sendEmail={sendEmail}
        />

        <GroupsTab
          accessType={accessType}
          selectedGroups={groupIds}
          setAccessType={setAccessType}
          setSelectedGroups={setGroupIds}
          selectedPartners={selectedPartners}
          setSelectedPartners={setSelectedPartners}
        />
      </SwipeTabs>

      <Footer
        formId="user-form-id"
        onCancel={() => setShowSaveModal(false)}
        onConfirm={handleSubmit}
        isLoading={isUpdating === "PENDING" || isCreating === "PENDING"}
        entity={translate("entities.users.plural")}
        entityName={formData.name}
        showModal={showSaveModal}
        modalType={editMode ? "update" : "create"}
      />
    </>
  );
}

UserForm.propTypes = {
  user: PropTypes.object,
};

UserForm.defaultProps = {
  user: null,
};

export default UserForm;
