import api from "~/shared/services/api";

export const getUsersReportCount = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/user/report/count", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );

export const getUsersReportCountOnline = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/user/report/count_online", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );

export const validateUserImport = ({ importData }) =>
  Promise.resolve(
    api.post("/admin/user/import/validate", {
      import_data: importData,
    })
  );

export const validateUserImportSingleData = ({ importData }) =>
  Promise.resolve(api.post("/admin/user/import/single_validate", importData));

export const importUserSheet = ({ importData, config }) =>
  Promise.resolve(
    api.post(`/admin/user/import`, {
      config,
      import_data: importData,
    })
  );

export const listUserImportsLog = ({ page, search }) =>
  Promise.resolve(
    api.get(`/admin/import_log`, {
      params: {
        type: "users",
        search,
        page,
      },
    })
  );

export const getUserImportLog = ({ importId }) =>
  Promise.resolve(api.get(`/admin/import_log/${importId}`));
