import React from "react";
import PropTypes from "prop-types";

//* VARIANTS
import {
  DiamondPartnerCell,
  GoldPartnerCell,
  SilverPartnerCell,
} from "./variants";

//* CUSTOM IMPORTS
import { tagGroupPropTypes, tagGroupDefaultProps } from "../../../molecules";
import { crudOptionsMenuPropTypes } from "../../CRUDOptionsMenu";

const partnerCellPropTypes = {
  title: PropTypes.string.isRequired,

  subTitle: PropTypes.string,

  bio: PropTypes.string,

  /** The URL for the avatar image */
  avatar: PropTypes.string,

  /** The Url for the banner image.
   *
   *  <i>obs: This props is used only in <b>diamond</b> variant</i>
   */
  banner: PropTypes.string,

  tags: tagGroupPropTypes.tags,

  /** Control the variants, default variant is <b>silver</b> */
  variant: PropTypes.oneOf(["silver", "gold", "diamond"]),

  /** Control all props of the <b>CRUDOptions</b> component,
   * see all props into <b>Organisms/CRUDOptionsMenu</b>
   */
  crudOptions: PropTypes.shape(crudOptionsMenuPropTypes),

  /** Callback that is triggered when users click into the Cell */
  onClick: PropTypes.func,

  /** Render a element, default is null */
  customButton: PropTypes.element,
};

const partnerCellDefaultProps = {
  subTitle: null,
  bio: null,
  avatar: null,
  banner: null,
  tags: tagGroupDefaultProps.tags,
  crudOptions: null,
  variant: "silver",
  onClick: () => {},
  customButton: null,
};

/** PartnerCell is used to display an Partner based on his level */
const PartnerCell = ({
  variant,
  title,
  subTitle,
  bio,
  avatar,
  banner,
  tags,
  crudOptions,
  onClick,
  customButton,
  ...rest
}) => {
  switch (variant) {
    case "gold":
      return (
        <GoldPartnerCell
          title={title}
          subTitle={subTitle}
          bio={bio}
          avatar={avatar}
          tags={tags}
          crudOptions={crudOptions}
          customButton={customButton}
          onClick={onClick}
          {...rest}
        />
      );

    case "diamond":
      return (
        <DiamondPartnerCell
          title={title}
          subTitle={subTitle}
          bio={bio}
          avatar={avatar}
          tags={tags}
          banner={banner}
          crudOptions={crudOptions}
          customButton={customButton}
          onClick={onClick}
          {...rest}
        />
      );

    default:
      return (
        <SilverPartnerCell
          title={title}
          subTitle={subTitle}
          avatar={avatar}
          tags={tags}
          crudOptions={crudOptions}
          customButton={customButton}
          onClick={onClick}
          {...rest}
        />
      );
  }
};

PartnerCell.propTypes = partnerCellPropTypes;

PartnerCell.defaultProps = partnerCellDefaultProps;

export { PartnerCell, partnerCellPropTypes, partnerCellDefaultProps };
