import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Icon, Button, Line } from "../../atoms";
import {
  modalPropTypes,
  modalDefaultProps,
  FooterButtons,
  footerButtonsPropTypes,
} from "../../molecules";
import { DefaultForm, Modal } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const formModalPropTypes = {
  /** Set Modal visibility */
  show: modalPropTypes.show,

  /** Close modal function */
  onClose: modalPropTypes.onClose,

  /** Render a title */
  title: modalPropTypes.title,

  /** String color for the title */
  titleColor: modalPropTypes.titleColor,

  /** Name of the modal class (string) */
  className: modalPropTypes.className,

  /** Object containing the style */
  style: modalPropTypes.style,

  /** Add an scroll for Modal */
  withScroll: modalPropTypes.withScroll,

  /** Children element form <b>Form</b> */
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,

  /** If this props has data control the <b>FooterButtons</b> props, see all
   * props in <b>Molecules/FooterButtons</b>
   */
  footerButtons: PropTypes.shape(footerButtonsPropTypes),

  isLoading: footerButtonsPropTypes.isLoading,

  /** Callback that is triggered when user click into <b>Delete</b> button, */
  onDelete: PropTypes.func,

  isDeleting: PropTypes.bool,

  /** If <b>true</b> render an <b>DeleteButton</b. */
  enableDeleteButton: PropTypes.bool,

  /** If this component has Data, change the <b>Form of this component</b>, for
   * this props.
   */
  formComponent: PropTypes.element,
};

const formModalDefaultProps = {
  titleColor: modalDefaultProps.titleColor,
  withScroll: modalDefaultProps.withScroll,
  show: false,
  onClose: modalDefaultProps.onClose,
  title: modalDefaultProps.title,
  className: modalDefaultProps.className,
  style: modalDefaultProps.style,
  footerButtons: null,
  onDelete: () => {},
  isDeleting: false,
  enableDeleteButton: false,
  isLoading: false,
  formComponent: null,
};

/** FormModal component is an Form inside an Modal, to see all the props that Form can have, access
 * <b>molecules/Form</b>
 */
const FormModal = ({
  show,
  title,
  withScroll,
  onClose,
  titleColor,
  className,
  style,
  children,
  footerButtons,
  isLoading,
  onDelete,
  isDeleting,
  enableDeleteButton,
  formComponent: FormComponent,
  ...rest
}) => {
  const { translate } = useTranslation();

  //* STATES
  const [confirmDelete, setConfirmDelete] = React.useState(false);

  const Form = FormComponent || DefaultForm;

  return (
    <Modal
      show={show}
      title={title}
      titleColor={titleColor}
      onClose={onClose}
      className={className}
      style={style}
      withScroll={withScroll}
    >
      <Form {...rest} className="form-modal-form">
        <Line style={{ marginBottom: 16 }} />
        {children}

        <footer className="form-modal-footer">
          {enableDeleteButton && (
            <Button
              variant="error"
              size="small"
              disableElevation
              icon={
                <Icon
                  iconName={confirmDelete ? "info" : "delete"}
                  variant="white"
                />
              }
              loading={isDeleting}
              onClick={confirmDelete ? onDelete : () => setConfirmDelete(true)}
            >
              {translate(confirmDelete ? "common.confirm" : "common.delete")}
            </Button>
          )}

          <FooterButtons
            {...footerButtons}
            className="form-modal-footer-buttons"
            onCancel={onClose}
            isLoading={isLoading}
            type="submit"
          />
        </footer>
      </Form>
    </Modal>
  );
};

FormModal.propTypes = formModalPropTypes;

FormModal.defaultProps = formModalDefaultProps;

export { FormModal };
