import produce from "immer";

// CUSTOM IMPORTS
import types from "./types";

const INITIAL_STATE = {
  // SPONSORED ROOMS
  sponsoredRooms: {
    data: [],
    isLoaded: false,
  },

  // TRANSMISSION ROOM
  transmissionRoom: {
    data: {},
    isLoaded: false,
    isQuestionSent: false,
    roomRate: {
      data: {},
      isLoaded: false,
    },
    isPrivate: false,
    theaterMode: false,
  },
};

export default function roomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // FETCH TRANSMISSION ROOM
      case "@rooms/FETCH_TRANSMISSION_ROOM_SUCCESSFUL": {
        const { transmissionRoom } = action.payload;

        if (transmissionRoom !== undefined && transmissionRoom !== null)
          draft.transmissionRoom.data = transmissionRoom;

        draft.transmissionRoom.isLoaded = true;
        break;
      }

      case "@rooms/SET_BROADCAST_ROOM_PRIVATE": {
        draft.transmissionRoom.isPrivate = true;

        break;
      }

      // ACTIVE THEATHER MODE
      case "@rooms/ACTIVE_THEATER_MODE": {
        draft.transmissionRoom.theaterMode = true;

        break;
      }

      case "@rooms/DEACTIVE_THEATER_MODE": {
        draft.transmissionRoom.theaterMode = false;

        break;
      }

      // CLEAR BROADCAST ROOM
      case types.BROADCAST_ROOM.CLEAR: {
        draft.transmissionRoom.data = {};
        draft.transmissionRoom.isLoaded = false;

        draft.transmissionRoom.roomRate.data = {};
        draft.transmissionRoom.roomRate.isLoaded = false;
        draft.transmissionRoom.theaterMode = false;
        draft.transmissionRoom.isPrivate = false;

        break;
      }

      // FETCH SPONSORED ROOMS
      case "@rooms/FETCH_SPONSORED_ROOMS_SUCCESSFUL": {
        const { sponsoredRooms } = action.payload;

        if (sponsoredRooms !== undefined && sponsoredRooms !== null)
          draft.sponsoredRooms.data = sponsoredRooms;

        draft.sponsoredRooms.isLoaded = true;

        break;
      }

      case "@rooms/CLEAR_SPONSORED_ROOMS": {
        draft.sponsoredRooms.data = [];

        draft.sponsoredRooms.isLoaded = false;

        break;
      }

      // FETCH TRANSMISSION ROOM RATE
      case "@rooms/FETCH_TRANSMISSION_ROOM_RATE_SUCCESSFUL": {
        const { roomRate } = action.payload;

        if (roomRate !== null && roomRate !== undefined)
          draft.transmissionRoom.roomRate.data = roomRate;

        draft.transmissionRoom.roomRate.isLoaded = true;

        break;
      }

      // SEND QUESTION
      case "@rooms/SEND_ROOM_QUESTION_SUCCESSFUL": {
        draft.transmissionRoom.isQuestionSent = true;

        break;
      }

      case "@rooms/CLEAR_ROOM_QUESTION": {
        draft.transmissionRoom.isQuestionSent = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT":
      case "@flux/CLEAR_REDUCER": {
        draft.transmissionRoom.roomRate.data = {};
        draft.transmissionRoom.isLoaded = false;
        draft.transmissionRoom.isQuestionSent = false;
        draft.transmissionRoom.isPrivate = false;
        draft.transmissionRoom.roomRate.isLoaded = false;
        draft.transmissionRoom.theaterMode = false;

        draft.sponsoredRooms.data = [];
        draft.sponsoredRooms.isLoaded = false;

        break;
      }
      default:
    }
  });
}
