import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  createBroadcastRoomScheduleRate,
  showBroadcastRoomScheduleRate,
} from "~/modules/signed/modules/client/apis/broadcastRooms/schedule/rate";

//* CUSTOM IMPORTS
import { getFullHours } from "~/shared/utils/dates";
import { Stars } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import {
  Modal,
  Title,
  Times,
  Time,
  Description,
  Scroll,
  Content,
  Speakers,
} from "./styles";
import { BookmarkButton } from "../../buttons/BookmarkButton";

function ScheduleDetailsModal({ roomId, schedule, onClose }) {
  const { translate } = useTranslation();

  //* STATES
  const [show, setShow] = React.useState(true);
  const [isRateLoaded, setIsRateLoaded] = React.useState(false);
  const [scheduleRate, setScheduleRate] = React.useState(0);

  //* FUNCTIONS
  React.useEffect(() => {
    const showScheduleRate = async () => {
      await showBroadcastRoomScheduleRate({
        scheduleId: schedule.id,
      }).then(response => {
        setScheduleRate(response.data);
        setIsRateLoaded(true);
      });
    };

    showScheduleRate();
  }, [roomId, schedule.id]);

  function handleOnClose() {
    setShow(false);

    setTimeout(() => onClose(), 300);
  }

  const evaluateSchedule = async rate => {
    await createBroadcastRoomScheduleRate({
      rate,
      scheduleId: schedule.id,
      roomId,
    });
  };

  return (
    <Modal show={show} onClose={handleOnClose}>
      <Scroll>
        <Title>{schedule.title}</Title>
        <Times>
          <Time>{getFullHours(schedule.start_time)} -</Time>
          <Time>{getFullHours(schedule.end_time)}</Time>
        </Times>

        <Stars
          title={translate("common.rateThisSchedule")}
          onClick={rate => evaluateSchedule(rate)}
          initialRate={scheduleRate && scheduleRate.rate}
          isLoaded={isRateLoaded}
        />

        <Content>
          {schedule.description && (
            <Description disabled defaultValue={schedule.description} />
          )}

          <Speakers
            speakers={schedule.speakers}
            renderCustomButton={s => (
              <BookmarkButton
                isSelected={s.bookmark}
                apiType="speakers"
                apiId={s.id}
              />
            )}
          />
        </Content>
      </Scroll>
    </Modal>
  );
}

ScheduleDetailsModal.propTypes = {
  schedule: PropTypes.object.isRequired,
  roomId: PropTypes.number.isRequired,
  onClose: PropTypes.func,
};

ScheduleDetailsModal.defaultProps = {
  onClose: () => {},
};

export default ScheduleDetailsModal;
