import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import SponsoredRooms from "./SponsoredRoomList";
import {
  Typography,
  TextArea,
  Trans,
  IFrame,
  Box,
} from "~/shared/components/atoms";
import {
  Container,
  AboutFragment,
  ContentContainer,
  PlyrPlayer,
} from "./styles";

function MarketRoomDetails({ partner }) {
  // DISRUPTING PARTNER
  const { bio, video_url: videoUrl, video_type: videoType, embeds } =
    partner && partner;

  return (
    <Container id="market-room-details">
      <AboutFragment>
        {bio && (
          <>
            <Typography className="title accent" style={{ marginBottom: 10 }}>
              <Trans translationKey="common.about">Sobre</Trans>
            </Typography>
            <TextArea disabled className="partner-bio" value={bio} />
          </>
        )}

        <SponsoredRooms partnerId={partner.id} />
      </AboutFragment>

      <ContentContainer>
        <PlyrPlayer type={videoType} url={videoUrl} plyrId="market-room-plyr" />

        {embeds && (
          <Box style={{ marginTop: 32 }}>
            {embeds.map(item => (
              <div key={item.id}>
                <Typography
                  className="subTitle bold"
                  style={{ marginBottom: 8 }}
                >
                  {item.title ? item.title : " "}
                </Typography>

                <IFrame
                  key={`partner-iframe-${item.id}`}
                  src={item.url}
                  height={`${item.height}px`}
                />
              </div>
            ))}
          </Box>
        )}
      </ContentContainer>
    </Container>
  );
}

MarketRoomDetails.propTypes = {
  partner: PropTypes.object.isRequired,
};

export default MarketRoomDetails;
