import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import { CellList, Empty } from "~/shared/components/molecules";
import { Typography } from "~/shared/components/atoms";
import { openFolderDetailsMaterials } from "~/shared/routes/navigation";
import {
  FolderMaterialCell,
  DownloadMaterialCell,
} from "~/shared/components/organisms/cells";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { isDataValid } from "~/shared/utils/validations";
import {
  UserDownloadMaterialsProvider,
  useDownloadMaterial,
} from "../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { Container } from "./styles";

function FolderDetailList() {
  const {
    materials,
    folders,
    isLoaded,
    showMaterial,
    listFolders,
    listMaterial,
  } = useDownloadMaterial();

  const { folderId } = useParams();
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderId)) {
      listFolders(folderId);
      listMaterial(folderId);
    }

    return () => {
      isMounted = false;
    };
  }, [listFolders, listMaterial, folderId, showMaterial]);

  const renderCellMaterials = cell => (
    <DownloadMaterialCell
      isBlocked={!cell.allow_access}
      title={cell.title}
      url={cell.file}
      fileSize={cell.file_size}
      fileType={cell.file_type}
      thumbnail={cell.thumbnail}
      onDownload={() => showMaterial(cell.id)}
    />
  );

  const renderCellFolder = folder => (
    <FolderMaterialCell
      title={folder.title}
      isBlocked={!folder.allow_access}
      onClick={() => openFolderDetailsMaterials(folder.id)}
    />
  );

  return (
    <Container>
      {folders.length !== 0 && (
        <CellList
          headerComponent={
            <Typography
              className="subTitle accent bold"
              style={{
                margin: "0 0 10px",
              }}
            >
              {translate(
                "entities.downloadMaterials.downloadMaterialList.folders"
              )}
            </Typography>
          }
          data={folders}
          renderCell={renderCellFolder}
          loaderListSize={4}
          gap={20}
          renderCellLoader={() => (
            <CellLoader width={300} height={243} style={{ height: 243 }} />
          )}
          cellStyle={{ marginBottom: 20 }}
          isLoaded={isLoaded}
        />
      )}

      <CellList
        headerComponent={
          <Typography
            className="subTitle accent bold"
            style={{
              margin: "0 0 10px",
            }}
          >
            {translate(
              "entities.downloadMaterials.downloadMaterialList.materials"
            )}
          </Typography>
        }
        data={materials}
        renderCell={renderCellMaterials}
        loaderListSize={4}
        gap={20}
        renderCellLoader={() => (
          <CellLoader width={300} height={243} style={{ height: 243 }} />
        )}
        isLoaded={isLoaded}
        emptyComponent={
          <Empty
            style={{
              position: "relative",
              bottom: 0,
              alignItems: "flex-start",
            }}
            message={translate("entities.downloadMaterials.emptyList.title")}
            description={translate(
              "entities.downloadMaterials.emptyList.description"
            )}
          />
        }
      />
    </Container>
  );
}

const DownloadMaterialList = () => {
  return (
    <UserDownloadMaterialsProvider>
      <FolderDetailList />
    </UserDownloadMaterialsProvider>
  );
};

export default DownloadMaterialList;
