import React from "react";

//* CUSTOM IMPORTS
import { Chat } from "./components/Chat";
import { CellList } from "~/shared/components/molecules";
import { useUserChat } from "../..";
import { Container, AvatarFrame } from "./styles";

const OpenedChatList = () => {
  const { openedsChats, expandChat, closeChat } = useUserChat();

  //* FUNCTIONS
  const renderCell = (chat, index) => {
    const { title, image, id, isMinimized, messageCounter, userIds } = chat;

    if (isMinimized)
      return (
        <AvatarFrame
          key={id}
          index={index}
          avatar={image}
          counter={messageCounter}
          variant={messageCounter > 0 && "accent"}
          onClick={() => expandChat(id)}
          onClose={() => closeChat(id)}
        />
      );

    return (
      <Chat
        key={id}
        chatId={id}
        avatar={image}
        userName={title}
        userId={userIds[0]}
      />
    );
  };

  return (
    <Container>
      <CellList
        isLoaded
        data={openedsChats}
        renderCell={renderCell}
        gap={16}
        cellStyle={{ display: "flex" }}
      />
    </Container>
  );
};

export { OpenedChatList };
