import api from "~/shared/services/api";

export const createDropdownAttributes = ({ attributeData }) =>
  Promise.resolve(api.post("/admin/dropdown_attribute", attributeData));

export const listDropdownAttributes = ({
  attribute_ref = null,
  attribute_dependence = null,
}) =>
  Promise.resolve(
    api.get("/admin/dropdown_attribute", {
      params: {
        attribute_ref,
        attribute_dependence,
      },
    })
  );

export const deleteDropdownAttribute = ({ attributeId }) =>
  Promise.resolve(api.delete(`/admin/dropdown_attribute/${attributeId}`));
