import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { ProfileConnectionCell } from "./styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { CONNECTION_TYPES } from "~/shared/utils/types";
import CheckListCellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { SearchBar } from "../../styles";

function ConnectionRequestTab({ callConnections }) {
  // STATES
  const { translate } = useTranslation();
  const {
    pendingConnections,
    listConnections,
    setPendingConnections,
    updateConnection,
    paginateConnections,
    profileNavigation,
    isUserLoaded,
    pendingCurrentPage,
    isSearching,
    searchString,
    setSearchString,
  } = useProfile();

  // FUNCTIONS

  React.useEffect(() => {
    if (!pendingConnections.isLoaded && callConnections)
      listConnections(
        pendingConnections.page,
        CONNECTION_TYPES.PENDING,
        setPendingConnections
      );
  }, [
    callConnections,
    listConnections,
    pendingConnections.isLoaded,
    pendingConnections.page,
    setPendingConnections,
  ]);

  const renderCell = pending => {
    return (
      <ProfileConnectionCell
        infos={{ ...pending, title: pending.name }}
        avatar={{ src: pending.avatar, size: 68 }}
        onClick={() => profileNavigation(pending)}
        footerButtons={{
          saveButton: translate("entities.profile.body.connection.connect"),
          cancelButton: translate("entities.profile.body.connection.refuse"),
          onClick: () =>
            updateConnection(
              pending.id,
              CONNECTION_TYPES.CONNECTED,
              CONNECTION_TYPES.PENDING
            ),
          onCancel: () =>
            updateConnection(
              pending.id,
              CONNECTION_TYPES.DISCONNECTED,
              CONNECTION_TYPES.PENDING
            ),
        }}
      />
    );
  };

  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate(
            "entities.profile.body.connection.emptyRequestsMessage"
          )}
          description={translate(
            "entities.profile.body.connection.emptyRequestsDescription"
          )}
          icon={<Icon iconName="userAdd" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="friends" />}
      />
    );
  };

  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);
          listConnections(
            pendingConnections.page,
            CONNECTION_TYPES.PENDING,
            setPendingConnections,
            value
          );
        }}
      />
      <CellList
        isLoaded={
          isUserLoaded &&
          !pendingConnections.isPaging &&
          !isSearching &&
          pendingConnections.isLoaded
        }
        renderCellLoader={() => (
          <CheckListCellLoader
            avatarSize={68}
            height={68}
            infosX={90}
            style={{ marginRight: "160px", marginBottom: "32px" }}
          />
        )}
        emptyComponent={handleEmptyMessage()}
        data={pendingConnections.data}
        renderCell={pending => renderCell(pending)}
        loaderListSize={9}
        gap={16}
      />

      <Pagination
        currentPage={pendingCurrentPage}
        handlePagination={page => {
          paginateConnections(CONNECTION_TYPES.PENDING, page);
        }}
        lastPage={pendingConnections.lastPage}
      />
    </div>
  );
}

ConnectionRequestTab.propTypes = {
  callConnections: PropTypes.bool.isRequired,
};

ConnectionRequestTab.defaultProps = {};

export default ConnectionRequestTab;
