import types from "./types";

// LIST
export function listPartners() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listPartnersSuccessful(partners) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { partners },
  };
}

// SHOW
export function showPartner(partnerId) {
  return {
    type: types.SHOW.REQUEST,
    payload: { partnerId },
  };
}
export function showPartnerSuccessful(partner) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { partner },
  };
}

// CREATE
export function craetePartner(partner, marketRoomId) {
  return {
    type: types.CREATE.REQUEST,
    payload: { partner, marketRoomId },
  };
}
export function craetePartnersSuccessful(partner) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { partner },
  };
}
export function craetePartnersFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updatePartner(partner, partnerId, marketRoomId) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { partner, partnerId, marketRoomId },
  };
}
export function updatePartnersSuccessful(partner) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { partner },
  };
}
export function updatePartnersFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deletePartner(partnerId, marketRoomId) {
  return {
    type: types.DELETE.REQUEST,
    payload: { partnerId, marketRoomId },
  };
}
export function deletePartnersSuccessful(partnerId, marketRoomId) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { partnerId, marketRoomId },
  };
}
export function deletePartnersFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// FORM
export function clearPartnerForm() {
  return {
    type: types.FORM.CLEAR,
  };
}
export function setTagIds(tagIds) {
  return {
    type: types.FORM.SET_TAGS_IDS,
    payload: { tagIds },
  };
}

// CLEAR
export function clearPartner() {
  return {
    type: types.OBJECT.CLEAR,
  };
}
