/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { useImportUsers, TABS_INDEX } from "../../hooks";
import { useCMS } from "../../../../../../../../hooks";
import { useAlert } from "~/shared/hooks/alert";

import ColumnModal from "./components/ColumnModal";
import ErrorsModal from "./components/ErrorsModal";

import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { Typography, LinkButton, Box } from "~/shared/components/atoms";
import { CellList } from "~/shared/components/molecules";
import { ImportTemplate } from "~/shared/components/templates";
import { MatchInfoCard, LoadingModal } from "~/shared/components/organisms";
import { removeSnakeCase } from "~/shared/utils/strings";
import {
  openAdminUserImportSettings,
  openAdminUserImport,
} from "~/shared/routes/navigation";

const Columns = () => {
  const { setFooterButtons } = useCMS();
  const { translate } = useTranslation();
  const {
    cadasterItems,
    sheetData,
    sheetColumns,
    importErrors,
    isValidating,
    validationFailed,
    validateSheetImport,
    validationStatus,
    VALIDATION_STATUS,
    tabIndex,
    dataToBeImported,
    clearCorrectedErrors,
    requiredCadasterItems,
    isImportErrorsChanged,
  } = useImportUsers();
  const { showAlert } = useAlert();

  const isValidated = validationStatus === VALIDATION_STATUS.VALIDATED;
  const isNotValidated = validationStatus === VALIDATION_STATUS.NOT_VALIDATED;
  const hasRequiredCadasterItems = requiredCadasterItems.length > 0;

  //* STATES
  const [showErrorModal, setShowErrorModal] = React.useState(false);
  const [showColumnModal, setShowColumnModal] = React.useState(false);
  const [currentColunm, setCurrentColunm] = React.useState([]);

  //* FUNCTIONS
  React.useMemo(() => {
    //* *If sheet passed into validation and has no errors, send user into next page */
    if (isValidated) openAdminUserImportSettings();
  }, [isValidated]);

  const handleOnConfirmClick = React.useCallback(() => {
    if (isNotValidated) {
      validateSheetImport();
      return;
    }

    //* *If sheet has already been validated and has 0 line valid show an alert */
    if (dataToBeImported() === 0) {
      showAlert({
        title: translate("entities.users.noUserWillBeImported"),
        description: translate("common.thereAreErrorsThatNeedToBeResolved"),
        confirmButton: translate("common.tapToResolve"),
        onConfirm: () => setShowErrorModal(true),
        onlyConfirm: true,
      });

      return;
    }

    //* *If sheet has already been validated and has some line erros show an alert */
    if (importErrors.length > 0) {
      showAlert({
        title: translate("common.alert"),
        description: (
          <>
            <strong>{importErrors.length}</strong>
            {`${translate(
              importErrors.length > 1
                ? "entities.users.usersWillNotBeImported"
                : "entities.users.userWillNotBeImported"
            )} ${translate("common.areYoSureYouWantToContinue")}`}
          </>
        ),
        confirmButton: translate("common.advance"),
        onConfirm: openAdminUserImportSettings,
      });

      return;
    }

    openAdminUserImportSettings();
  }, [isNotValidated, dataToBeImported, importErrors]);

  React.useEffect(() => {
    if (tabIndex === TABS_INDEX.MATCH)
      setFooterButtons({
        isVisible: true,
        saveButton: isNotValidated
          ? translate("common.validate")
          : translate("common.advance"),
        cancelButton: translate("common.return"),
        onClick: handleOnConfirmClick,
        onCancel: openAdminUserImport,
        isDisable: hasRequiredCadasterItems,
      });
  }, [
    handleOnConfirmClick,
    isNotValidated,
    setFooterButtons,
    tabIndex,
    validateSheetImport,
  ]);

  React.useMemo(() => {
    //* *If is is the first time the import errors is filled open the errors modal */
    if (importErrors.length > 0 && !isImportErrorsChanged) {
      setShowErrorModal(true);
    } else {
      setShowErrorModal(false);
    }
  }, [importErrors]);

  const renderItems = header => {
    const { correspondingField } = header;

    return sheetData.slice(0, 4).map(d => {
      if (correspondingField) return d[correspondingField.ref_code];

      return d[header.name];
    });
  };

  const openColumnModal = header => {
    setCurrentColunm(header);
    setShowColumnModal(true);
  };

  const closeColumnModal = () => {
    setCurrentColunm(null);
    setShowColumnModal(false);
  };

  const renderMatchInfoCardVariant = header => {
    if (header.correspondingField) return "checked";

    if (hasRequiredCadasterItems) return "error";

    return "warning";
  };

  const renderMatchInfoCardMessage = () => {
    if (hasRequiredCadasterItems)
      return (
        <>
          {translate("pages.userImportMatchInfo.errorColumnMessage.part1")}
          <br />
          {translate("pages.userImportMatchInfo.errorColumnMessage.part2")}
          <br />
          {translate("pages.userImportMatchInfo.errorColumnMessage.part3")}
          <br />
        </>
      );

    return (
      <>
        {translate("pages.userImportMatchInfo.wargingColumMessage.part1")}.
        <br />
        {translate("pages.userImportMatchInfo.wargingColumMessage.part2")}
        <br />
        {translate("pages.userImportMatchInfo.wargingColumMessage.part3")}
        <br />
      </>
    );
  };

  const renderCell = header => (
    <MatchInfoCard
      key={header.id}
      variant={renderMatchInfoCardVariant(header)}
      title={removeSnakeCase(header.name)}
      simpleMenu={{
        children: (
          <>
            {renderMatchInfoCardMessage()}
            <LinkButton type="button" onClick={() => openColumnModal(header)}>
              {translate("common.tapToResolve")}
            </LinkButton>
          </>
        ),
      }}
      onEdit={() => openColumnModal(header)}
      style={{ width: 280 }}
      items={renderItems(header)}
      itemEmpty={
        <Typography className="opaque italic">
          {translate("common.unavailable")}
        </Typography>
      }
    />
  );

  const renderColumnsCounter = willBeImported => {
    let counter = 0;

    sheetColumns.forEach(header => {
      if (willBeImported) {
        if (header.correspondingField) counter += 1;
      } else if (!header.correspondingField) counter += 1;
    });

    return counter;
  };

  return (
    <>
      <ImportTemplate
        title={translate("entities.users.matchUserInfo")}
        subTitle={
          <>
            <strong>{dataToBeImported()}</strong>{" "}
            {translate(
              dataToBeImported() > 1
                ? "entities.users.usersWillBeImported"
                : "entities.users.userWillBeImported"
            )}
            {importErrors.length > 0 && (
              <LinkButton
                variant="error"
                onClick={() => setShowErrorModal(true)}
                style={{ marginLeft: 8 }}
              >
                {importErrors.length}{" "}
                {translate(
                  importErrors.length > 1
                    ? "entities.users.usersWillNotBeImported"
                    : "entities.users.userWillNotBeImported"
                )}
                . {translate("common.tapToCheck")}
              </LinkButton>
            )}
          </>
        }
      >
        <>
          <CellList
            withScroll
            isLoaded
            data={sheetColumns}
            renderCell={renderCell}
            gap={20}
            renderCellLoader={() => (
              <CellLoader
                width={280}
                height={200}
                style={{ width: 280, height: "100%" }}
              />
            )}
          />

          <Box marginTop="40px">
            <Box display="flex">
              {renderColumnsCounter(true) > 0 && (
                <Typography style={{ marginRight: 4 }}>
                  <strong>{renderColumnsCounter(true)}</strong>{" "}
                  {translate(
                    renderColumnsCounter(true) > 1
                      ? "common.columnsWillBeImported"
                      : "common.columnWillBeImported"
                  )}
                  .
                </Typography>
              )}

              {renderColumnsCounter(false) > 0 && (
                <Typography>
                  <strong>{renderColumnsCounter(false)}</strong>{" "}
                  {translate(
                    renderColumnsCounter(false) > 1
                      ? "common.columnsWillNotBeImported"
                      : "common.columnWillNotBeImported"
                  )}
                </Typography>
              )}
            </Box>

            {hasRequiredCadasterItems && (
              <Box marginTop="20px">
                {requiredCadasterItems.map(item => (
                  <Typography className="error" style={{ marginBottom: 8 }}>
                    {translate("common.YouNeedToImportAColumnWithTheName")}{" "}
                    <strong>{item.title}</strong>
                  </Typography>
                ))}
              </Box>
            )}
          </Box>
        </>
      </ImportTemplate>

      <LoadingModal
        show={isValidating}
        title={translate("common.validatingImport")}
        description={translate("common.lookingForPossibleErrors")}
        errorMessage={
          validationFailed &&
          `${translate("common.validationFailed")} ${translate(
            "common.tryAgainInAFewMinutes"
          )}`
        }
      />

      {showErrorModal && importErrors.length > 0 && (
        <ErrorsModal
          show={showErrorModal}
          onClose={() => {
            setShowErrorModal(false);
          }}
          onSave={() => {
            setShowErrorModal(false);

            clearCorrectedErrors();
          }}
        />
      )}

      {showColumnModal && (
        <ColumnModal
          cadasterItems={cadasterItems}
          column={currentColunm}
          show={showColumnModal}
          onClose={closeColumnModal}
        />
      )}
    </>
  );
};

function ImportUserMatchInformation() {
  const { sheetData } = useImportUsers();

  return sheetData.length > 0 && <Columns />;
}

export default ImportUserMatchInformation;
