import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useTranslation } from "../../../hooks/translation";
import { getFullHours } from "../../../utils/dates";
import {
  IconButton,
  Icon,
  Line,
  Box,
  Spinner,
  HoverWrapper,
} from "../../atoms";
import {
  VirtualizedList,
  ChatInput,
  virtualizedListPropTypes,
  Infos,
} from "../../molecules";
import { talkMessageCellPropTypes } from "../cells";
import { Card, Header, Buttons, TalkMessageCell, Day } from "./styles";

const individualChatPropTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,

      ...talkMessageCellPropTypes,

      /** If this props is <b>true</b> change the message card to <b>accent</b> */
      isMyMessage: PropTypes.bool,

      /** Render an <b>Marker</b> above the current message */
      marker: PropTypes.string,

      /** If this props is <b>true</b> change the message card <b>Icon</b> */
      isReceived: PropTypes.bool,

      /** If this props has data, add an reply message into Chat */
      repliedMessage: PropTypes.any.string,
    })
  ).isRequired,

  userName: PropTypes.string.isRequired,

  /** The url image for the user avatar */
  avatar: PropTypes.string,

  /** Callback that is triggerred when user scroll to the bottom of the Chat */
  onBottomReached: PropTypes.func,

  /** Callback that is triggerred when user scroll to the top of the Chat */
  onTopReached: PropTypes.func,

  /** Callback that is triggerred when user send an message. Return the value of the message.
   *
   * Example: (valueMessag) => {}
   */
  onSend: PropTypes.func,

  /** Callback that is triggered when user close the Chat */
  onClose: PropTypes.func,

  /** Callback that is triggered when user minimize the Chat */
  onMinimize: PropTypes.func,

  /** Callback that is triggered when user clicks to reply an message */
  onReply: PropTypes.func,

  /** Callback that is triggered when user clicks to close reply message */
  onReplyClose: PropTypes.func,

  isLoading: PropTypes.bool,

  /** If <b>true</b> render an <b>Spinner</b> at top of list */
  isPaging: PropTypes.bool,

  /** Disable all scroll events */
  disableScrollListener: virtualizedListPropTypes.disableScrollListener,

  /** To control the start index when an pagination occurs, or when the list is rendered */
  perPage: PropTypes.number,

  /** Id to track the input */
  inputId: PropTypes.string,

  /** Render an component if list is empty */
  emptyComponent: PropTypes.element,

  /** Callback to open user profile */
  onProfileOpen: PropTypes.func,

  virtualizedListCache: PropTypes.object,
};

const individualChatDefaultProps = {
  avatar: null,
  onBottomReached: () => {},
  onTopReached: () => {},
  onSend: () => {},
  onClose: () => {},
  onMinimize: () => {},
  onReply: () => {},
  onReplyClose: () => {},
  isLoading: false,
  isPaging: false,
  disableScrollListener: false,
  perPage: 0,
  inputId: null,
  emptyComponent: null,
  onProfileOpen: () => {},
  virtualizedListCache: null,
};

/** An individual chat for user */
const IndividualChat = ({
  data,
  userName,
  avatar,
  onBottomReached,
  onTopReached,
  onSend,
  onClose,
  onMinimize,
  onReply,
  onReplyClose,
  isLoading,
  isPaging,
  disableScrollListener,
  perPage,
  inputId,
  emptyComponent,
  onProfileOpen,
  virtualizedListCache,
  ...rest
}) => {
  //* STATES
  const [replyMessage, setReplyMessage] = React.useState(null);

  //* FUNCTIONS
  const { translate } = useTranslation();

  const handleOnSend = value => {
    setReplyMessage(null);
    onSend(value);
  };

  const handleReplyMessage = React.useCallback(
    message => {
      setReplyMessage(message);
      onReply(message);

      const input = document.getElementById(`chat-input-${inputId}`);

      if (input) {
        setTimeout(() => {
          input.focus();
        }, 1);
      }
    },
    [inputId, onReply]
  );

  const handleCloseReplyMessage = () => {
    setReplyMessage(null);
    onReplyClose();
  };

  const renderCell = message => {
    const {
      id: messageId,
      isMyMessage,
      time,
      isReaded,
      marker,
      repliedMessage,
    } = message;

    return (
      <Box position="relative">
        {marker && <Day>{marker}</Day>}

        <TalkMessageCell
          {...message}
          key={messageId}
          isMyMessage={isMyMessage}
          variant={isMyMessage && "accent"}
          time={getFullHours(time)}
          isReaded={isReaded}
          replyMessageCard={
            repliedMessage && {
              message: repliedMessage,
              disableCloseButton: true,
            }
          }
          simpleMenu={{
            fillItems: () => [
              {
                id: 1,
                name: translate("common.answer"),
                icon: <Icon iconName="send" />,
                function: () => handleReplyMessage(message),
              },
            ],
          }}
        />
      </Box>
    );
  };

  return (
    <Card {...rest}>
      <Header>
        <HoverWrapper onClick={onProfileOpen}>
          <Infos avatar={{ src: avatar }} title={userName} />
        </HoverWrapper>

        <Buttons>
          <IconButton
            size={32}
            onClick={onMinimize}
            title={translate("common.minimize")}
          >
            <Icon iconName="minimize" />
          </IconButton>

          <IconButton
            size={32}
            onClick={onClose}
            title={translate("common.close")}
          >
            <Icon iconName="close" />
          </IconButton>
        </Buttons>
      </Header>

      <Line />

      <Box display="flex" flex={1}>
        {isLoading ? (
          <Spinner centered />
        ) : (
          <VirtualizedList
            data={data}
            headerComponent={
              isPaging && (
                <Spinner
                  size="small"
                  centered
                  style={{ height: 30, margin: "8px 0" }}
                />
              )
            }
            renderCell={renderCell}
            onTopReached={onTopReached}
            onBottomReached={onBottomReached}
            style={{ paddingTop: 8, flex: 1 }}
            disableScrollListener={disableScrollListener}
            scrollToIndex={perPage}
            emptyComponent={emptyComponent}
            cache={virtualizedListCache}
          />
        )}
      </Box>

      <Line />

      <ChatInput
        withPreLine
        inputId={inputId}
        replyMessageCard={
          replyMessage && { ...replyMessage, onClose: handleCloseReplyMessage }
        }
        placeholder={translate("components.individualChat.input.placeholder")}
        onSubmit={handleOnSend}
        style={{ boxShadow: "unset", position: "relative", zIndex: 2 }}
      />
    </Card>
  );
};

IndividualChat.propTypes = individualChatPropTypes;

IndividualChat.defaultProps = individualChatDefaultProps;

export { IndividualChat, individualChatPropTypes, individualChatDefaultProps };
