import React from "react";
import PropTypes from "prop-types";
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from "react-icons/md";

//* CUSTOM IMPORTS
import { Container, ArrowButton, PageNumbers, PageButton } from "./styles";

const paginationPropTypes = {
  /** This number is needed to calculate the Pagination, the last page to paginate */
  lastPage: PropTypes.number.isRequired,

  /** Contorl the current page that is marked into Pagination */
  currentPage: PropTypes.number.isRequired,

  /** Callback that is trigered when user change clicks to change page,
   * return the page that users wants to go.
   * Example: (page) => {}
   */
  handlePagination: PropTypes.func,
};

const paginationDefaultProps = {
  handlePagination: () => {},
};

function Pagination({ lastPage, currentPage, handlePagination, ...rest }) {
  //* STATES
  const [currentPages, setCurrentPages] = React.useState([]);
  const [isMounted, setIsMounted] = React.useState(false);
  const [disableTrim, setDisableTrim] = React.useState(false);

  const MAX_PAGE_BUTTONS = 7;
  const TRIM_BUTTON = 0;

  const parsedCurrentPage = Number(currentPage);
  const parsedLastPage = Number(lastPage);

  React.useEffect(() => {
    if (parsedLastPage >= MAX_PAGE_BUTTONS) {
      if (parsedCurrentPage <= 3) {
        setDisableTrim(true);
        setCurrentPages([1, TRIM_BUTTON, 2, 3, 4, TRIM_BUTTON, parsedLastPage]);
      }

      if (parsedCurrentPage >= parsedLastPage - 2) {
        setDisableTrim(false);
        setCurrentPages([
          1,
          TRIM_BUTTON,
          parsedLastPage - 3,
          parsedLastPage - 2,
          parsedLastPage - 1,
          parsedLastPage,
        ]);
      }

      if (parsedCurrentPage > 3 && parsedCurrentPage < parsedLastPage - 2) {
        setDisableTrim(false);
        setCurrentPages([
          1,
          TRIM_BUTTON,
          parsedCurrentPage - 1,
          parsedCurrentPage,
          parsedCurrentPage + 1,
          TRIM_BUTTON,
          parsedLastPage,
        ]);
      }
    } else {
      const auxArray = [];

      for (let i = 1; i <= parsedLastPage; i += 1) {
        auxArray.push(i);
      }

      setCurrentPages(auxArray);
    }
    setIsMounted(true);
  }, [parsedCurrentPage, parsedLastPage]);

  function onChangePage(p) {
    handlePagination(p);
  }

  function nextPage() {
    if (parsedCurrentPage < parsedLastPage) onChangePage(parsedCurrentPage + 1);
  }

  function prevPage() {
    if (parsedCurrentPage > 1) onChangePage(parsedCurrentPage - 1);
  }

  function handleChangePage(page) {
    if (page !== TRIM_BUTTON) onChangePage(page);
  }

  function renderPageButton(page, index) {
    return (
      <PageButton
        key={`${page} - ${index}`}
        invisible={index === 1 && disableTrim}
        isDisabled={page === TRIM_BUTTON}
        isActive={parsedCurrentPage === page}
        onClick={() => handleChangePage(page)}
      >
        {page === TRIM_BUTTON ? "..." : page}
      </PageButton>
    );
  }

  return (
    isMounted &&
    lastPage > 0 && (
      <Container {...rest}>
        <ArrowButton
          type="button"
          onClick={prevPage}
          disabled={parsedCurrentPage === 1}
        >
          <MdKeyboardArrowLeft />
        </ArrowButton>

        <PageNumbers>
          {currentPages.map((page, index) => renderPageButton(page, index))}
        </PageNumbers>

        <ArrowButton
          type="button"
          onClick={nextPage}
          disabled={parsedCurrentPage === parsedLastPage}
        >
          <MdKeyboardArrowRight />
        </ArrowButton>
      </Container>
    )
  );
}

Pagination.propTypes = paginationPropTypes;

Pagination.defaultProps = paginationDefaultProps;

export { Pagination, paginationPropTypes, paginationDefaultProps };
