import React from "react";
import { MdClose } from "react-icons/md";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, Message, CloseButton } from "./styles";

const replyMessageCardPropTypes = {
  /** Render a string title */
  messageTitle: PropTypes.string.isRequired,

  /** Render a string message */
  message: PropTypes.string.isRequired,

  /** Color of the left border */
  color: PropTypes.string,

  /** Close handler */
  onClose: PropTypes.func,

  /** Removes the close component */
  disableCloseButton: PropTypes.bool,

  /** Add highlight if admin */
  hasHighlight: PropTypes.bool,

  variant: PropTypes.oneOf(["default", "shadeAccent"]),
};

const replyMessageCardDefaultProps = {
  color: null,
  onClose: () => {},
  disableCloseButton: false,
  hasHighlight: false,
  variant: "default",
};
/** ReplyMessageCard is commonly used for replys in broadcast room chats. */
const ReplyMessageCard = ({
  messageTitle,
  message,
  color,
  onClose,
  disableCloseButton,
  hasHighlight,
  variant,
  ...rest
}) => {
  return (
    <Container
      {...rest}
      variant={variant}
      hasHighlight={hasHighlight}
      borderLeft={color}
    >
      <Message hasHighlight={hasHighlight}>
        <>
          <strong style={{ marginRight: 4 }}>{messageTitle}</strong>
          {message}
        </>
      </Message>

      {!disableCloseButton && (
        <CloseButton
          title="Remover resposta"
          className="reply-card-remove-button"
          onClick={onClose}
        >
          <MdClose />
        </CloseButton>
      )}
    </Container>
  );
};

ReplyMessageCard.propTypes = replyMessageCardPropTypes;

ReplyMessageCard.defaultProps = replyMessageCardDefaultProps;

export {
  ReplyMessageCard,
  replyMessageCardPropTypes,
  replyMessageCardDefaultProps,
};
