import produce from "immer";

import { isDataValid, isListValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  object: {
    data: {},
    isLoaded: false,
    buttons: {
      list: [],
    },
    tagIds: [],
  },
};

export default function adminPartnersReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { partners } = action.payload;

        if (isDataValid(partners)) draft.list.data = partners;

        draft.list.isLoaded = true;

        break;
      }

      // SHOW
      case types.SHOW.SUCCESSFUL: {
        const { partner } = action.payload;

        if (isDataValid(partner)) {
          draft.object.data = partner;
          const { tags } = partner;

          // Fill tagIds
          if (isListValid(tags)) {
            const tagIds = tags.map(tag => tag.id);

            draft.object.tagIds = tagIds;
          }
        }

        draft.object.isLoaded = true;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { partner } = action.payload;

        if (isDataValid(partner))
          draft.list.data = [...draft.list.data, partner];

        draft.list.isCreating = "FINISH";
        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { partner } = action.payload;

        if (isDataValid(partner)) {
          const broadcastRooms = draft.list.data;
          const broadcastRoomIndex = broadcastRooms.findIndex(
            s => s.id === partner.id
          );

          broadcastRooms[broadcastRoomIndex] = partner;

          draft.list.data = broadcastRooms;
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { broadcastRoomId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== broadcastRoomId
        );

        draft.list.data = filteredArray;
        draft.list.isDeleting = "FINISH";

        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // FORM
      case types.FORM.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isLoaded = false;

        draft.object.data = {};
        draft.object.tagIds = [];
        draft.object.isLoaded = false;

        break;
      }

      case types.FORM.SET_TAGS_IDS: {
        const { tagIds } = action.payload;

        draft.object.tagIds = tagIds;

        break;
      }

      // CLEAR
      case types.OBJECT.CLEAR: {
        draft.object.data = {};
        draft.object.isLoaded = false;
        draft.object.buttons.list = [];
        draft.object.tagIds = [];

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isLoaded = false;

        draft.object.data = {};
        draft.object.tagIds = [];
        draft.object.isLoaded = false;

        break;
      }
      default:
    }
  });
}
