import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";

// ACTIONS

// CUSTOM IMPORTS
import { Button } from "~/shared/components/atoms";
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { SwipeTabs } from "~/shared/components/molecules";
import { FooterButtons } from "./styles";
import { useAdminEmail } from "../../hooks";
import { createDate, getFullDate, getFullHours } from "~/shared/utils/dates";
import { useTranslation } from "~/shared/hooks/translation";
import { openAdminEmails } from "~/shared/routes/navigation";
import { EMAIL_TYPES } from "~/shared/utils/types";

// TAB FRAGMENTS
import AspectTab from "./components/AspectTab";
import InformationTab from "./components/InformationTab";
import EmailAudience from "./components/AudienceTab";
import { useAlert } from "~/shared/hooks/alert";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function EmailForm({ email }) {
  const {
    showAlert,
    closeAlert,
    startAlertLoading,
    stopAlertLoading,
  } = useAlert();
  const { translate } = useTranslation();
  const { toJsonTranslation } = useDynamicTranslation();

  const textEditorInitialHtml = `<strong class="ql-size-large">${translate(
    "entities.emails.emailTitle"
  )}</strong><p>${translate("entities.emails.emailBody")}</p>`;

  const {
    createAdminEmail,
    updateAdminEmail,
    checkEmail,
    CHECK_STATUS,
    sender,
  } = useAdminEmail();

  // STATES
  const [formInitialData, setFormInitialData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);
  const [sendNow, setSendNow] = React.useState(false);
  const [selectedGroups, setSelectedGroups] = React.useState([]);
  const [emailBody, setEmailBody] = React.useState("");
  const [emailHeader, setEmailHeader] = React.useState(null);
  const [isSketch, setIsSketch] = React.useState(true);

  // FUNCTIONS
  React.useEffect(() => {
    /** Add the default sender */
    let initialData = {
      sender_name: sender.name,
      sender_email: sender.email,
      text: toJsonTranslation(textEditorInitialHtml),
    };

    if (email) {
      const { schedule_date: date, send_now, text } = email;

      if (email.groups) {
        const groupIds = email.groups.map(group => group.id);
        setSelectedGroups(groupIds);
      }

      setSendNow(send_now);
      setEmailBody(text);

      initialData = {
        ...email,
        scheduleDate: getFullDate(date),
        scheduleTime: getFullHours(date),
      };
    } else {
      setEmailBody(toJsonTranslation(textEditorInitialHtml));
    }

    setFormInitialData(initialData);
  }, [
    email,
    sender.email,
    sender.name,
    textEditorInitialHtml,
    toJsonTranslation,
  ]);

  const handleOnCancel = () => {
    showAlert({
      title: translate("common.cancel"),
      description: translate("entities.emails.emailModal.exit"),
      onConfirm: openAdminEmails,
    });
  };

  const getEmailType = () => {
    if (isSketch) return EMAIL_TYPES.SKETCH;

    if (sendNow) return EMAIL_TYPES.SENDED;

    return EMAIL_TYPES.SCHEDULE;
  };

  function getFooterLabel() {
    if (sendNow) {
      return translate("entities.emails.footer.send");
    }
    return translate("entities.emails.footer.schedule");
  }

  const handleOnSaveEmail = async data => {
    const { scheduleDate, scheduleTime } = data;

    let status = null;

    startAlertLoading();
    await checkEmail(data.sender_email).then(response => {
      status = response;
    });

    if (status === CHECK_STATUS.VERIFIED) {
      const formatedData = {
        ...data,
        text: emailBody,
        header_url: emailHeader,
        send_now: sendNow,
        type: getEmailType(),
        group_ids: selectedGroups,
        schedule_date: createDate({ date: scheduleDate, time: scheduleTime }),
      };

      if (email) {
        await updateAdminEmail(formatedData, email.id, email.type);
      } else {
        await createAdminEmail(formatedData);
      }

      openAdminEmails();
    }

    stopAlertLoading();
    closeAlert();
  };

  const handleOnSubmit = data => {
    showAlert(
      {
        title: isSketch
          ? translate("entities.emails.emailModal.saveSketch")
          : translate("entities.emails.emailModal.saveEmail"),
        description: isSketch
          ? translate("entities.emails.emailModal.saveSketchDescription")
          : translate("entities.emails.emailModal.saveEmailDescription"),
        onConfirm: () => {
          handleOnSaveEmail(data);
        },
      },
      true
    );
  };

  // TABS
  const tabs = [
    {
      id: 1,
      name: translate("common.appearance"),
    },
    { id: 2, name: translate("common.informations"), error: infoTabError },

    { id: 3, name: translate("common.public") },
  ];

  const timeInputSchema = !sendNow && {
    scheduleDate: Yup.string().required(
      translate("entities.emails.handlers.scheduleDate")
    ),
    scheduleTime: Yup.string().required(
      translate("entities.emails.handlers.scheduleTime")
    ),
  };

  const yupValidation = {
    name: Yup.string().required(translate("entities.emails.handlers.name")),
    about: Yup.string().required(translate("entities.emails.handlers.about")),
    description: Yup.string().required(
      translate("entities.emails.handlers.description")
    ),
    sender_email: Yup.string()
      .email(translate("handlers.email.isEmailInvalid"))
      .trim()
      .required(translate("handlers.email.isRequired")),
    sender_name: Yup.string().required(
      translate("entities.emails.handlers.senderName")
    ),
    ...timeInputSchema,
  };

  return (
    <>
      <Form
        yupValidation={
          isSketch
            ? {
                name: Yup.string().required("O título do email é obrigatório"),
                sender_email: Yup.string()
                  .email(translate("handlers.email.isEmailInvalid"))
                  .trim()
                  .required(translate("handlers.email.isRequired")),
                sender_name: Yup.string().required(
                  "O nome do remetente é obrigatório"
                ),
              }
            : yupValidation
        }
        id="email-form"
        onSubmit={handleOnSubmit}
        initialData={formInitialData}
        onError={() => setInfoTabError(true)}
      >
        <SwipeTabs size="small" alignment="left" tabs={tabs}>
          <AspectTab
            email={email}
            bodyValue={emailBody}
            headerValue={emailHeader}
            onTextEditorChange={value => setEmailBody(value)}
            onHeaderChange={value => setEmailHeader(value)}
          />

          <InformationTab sendNow={sendNow} setSendNow={setSendNow} />

          <EmailAudience
            selectedGroups={selectedGroups}
            setSelectedGroups={setSelectedGroups}
          />
        </SwipeTabs>
      </Form>

      <FooterButtons
        saveButton={getFooterLabel()}
        formId="email-form"
        onCancel={handleOnCancel}
        confirmButtonName="emailConfirmButton"
        onClick={() => setIsSketch(false)}
        middleButtonComponent={
          <Button
            variant="outlined"
            disableElevation
            form="email-form"
            type="submit"
            size="small"
            style={{ marginLeft: 8 }}
            onClick={() => setIsSketch(true)}
          >
            {translate("entities.emails.footer.saveSketch")}
          </Button>
        }
      />
    </>
  );
}

EmailForm.propTypes = {
  email: PropTypes.object,
};

EmailForm.defaultProps = {
  email: null,
};

export default EmailForm;
