import React from "react";
import PropTypes from "prop-types";

import { useTranslation } from "~/shared/hooks/translation";

import { Container, TranslateInput, FileInput } from "./styles";

function InformationTab({ fileUrl, onFileUpload }) {
  const { translate } = useTranslation();

  return (
    <Container id="download-materials-infos">
      <TranslateInput
        name="name"
        title={translate(
          "entities.downloadMaterials.informationTab.inputName.title"
        )}
        placeholder={translate(
          "entities.downloadMaterials.informationTab.inputName.placeholder"
        )}
      />
      <FileInput
        name="url"
        title={translate(
          "entities.downloadMaterials.informationTab.fileInput.title"
        )}
        width={300}
        heigth={168}
        onUpload={onFileUpload}
        src={fileUrl || null}
      />
    </Container>
  );
}

InformationTab.propTypes = {
  fileUrl: PropTypes.string,
  onFileUpload: PropTypes.func,
};

InformationTab.defaultProps = {
  fileUrl: null,
  onFileUpload: () => {},
};

export default InformationTab;
