import React from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";

// ACTIONS
import { updateUser } from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import { Box } from "~/shared/components/atoms";
import { isDataValid } from "~/shared/utils/validations";
import { UserGroups } from "./styles";

function GroupsTab({ user }) {
  const dispatch = useDispatch();

  // STATES
  const [groupIds, setGroupIds] = React.useState([]);

  // FUNCTIONS
  React.useEffect(() => {
    if (isDataValid(user)) {
      const { groups } = user;

      const auxArray = groups.map(group => group.id);

      setGroupIds(auxArray);
    }
  }, [user]);

  function handleSelectGroup(groups) {
    const userData = {
      ...user,
      ...user.attributes,
      groupIds: groups,
    };

    dispatch(updateUser(userData, user.id, true));
  }

  return (
    <Box>
      <UserGroups
        setSelectedGroups={setGroupIds}
        selectedGroups={groupIds}
        onSelect={handleSelectGroup}
      />
    </Box>
  );
}

GroupsTab.propTypes = {
  user: PropTypes.object.isRequired,
};

export default GroupsTab;
