import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  openAdminNotificationsReceived,
  openAdminNotificationsOpened,
} from "~/shared/routes/navigation";
import { DashboardReportCard, Empty } from "~/shared/components/molecules";
import { HoverWrapper, Icon } from "~/shared/components/atoms";
import { Container } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

//* APIs
import { getNotificationTotalCount } from "~/modules/signed/modules/cms/modules/admin/apis/notifications";

function NotificationsRoomPerformance({ notification }) {
  const { translate } = useTranslation();

  const [counterOpened, setCounterOpened] = React.useState(null);
  const [counterReceived, setCounterReceived] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);

  const isSent = notification && notification.sended_at;

  //* FUNCTIONS
  React.useEffect(() => {
    const get = async () => {
      await getNotificationTotalCount({
        action: "opened",
        notificationId: notification.id,
      }).then(response => {
        setCounterOpened(response.data.total_count);
        setIsLoaded(true);
      });
    };

    if (isSent) get();
  }, [notification.id, isSent]);

  React.useEffect(() => {
    const get = async () => {
      await getNotificationTotalCount({
        action: "received",
        notificationId: notification.id,
      }).then(response => {
        setCounterReceived(response.data.total_count);
      });
    };

    if (isSent) get();
  }, [notification.id, isSent]);

  return (
    <Container>
      {isSent ? (
        <>
          <HoverWrapper>
            <DashboardReportCard
              dotColor="#2BFD6A"
              reportItems={[
                {
                  id: 1,
                  counter: counterReceived || 0,
                  description: translate(
                    "entities.notifications.peopleReceived"
                  ),
                },
              ]}
              onClick={() =>
                openAdminNotificationsReceived({
                  notificationId: notification.id,
                })
              }
              isLoaded={isLoaded}
              style={{ marginLeft: 0 }}
            />
          </HoverWrapper>
          <HoverWrapper>
            <DashboardReportCard
              className="opened"
              dotColor="#2BFD6A"
              reportItems={[
                {
                  id: 1,
                  counter: counterOpened || 0,
                  description: translate("entities.notifications.peopleOpened"),
                },
              ]}
              onClick={() =>
                openAdminNotificationsOpened({
                  notificationId: notification.id,
                })
              }
              isLoaded={isLoaded}
              style={{ marginLeft: 16 }}
            />
          </HoverWrapper>{" "}
        </>
      ) : (
        <Empty
          style={{ margin: "0 24px" }}
          icon={<Icon iconName="notifications" />}
          message="Notificação não enviada"
          description="As informações de desempenho estarão disponíveis somenete após o envio"
        />
      )}
    </Container>
  );
}

NotificationsRoomPerformance.propTypes = {
  notification: PropTypes.object.isRequired,
};

export default NotificationsRoomPerformance;
