import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { stopPropagation } from "../../../utils/events";
import {
  Box,
  Button,
  Trans,
  buttonPropTypes,
  buttonDefaultProps,
  Icon,
} from "../../atoms";

const footerButtonsPropTypes = {
  /** Callback that is triggered when user clicks into <b>SaveButton</b> */
  onClick: PropTypes.func,

  /** Callback that is triggered when user clicks into <b>CancelButton</b> */
  onCancel: PropTypes.func,

  /** if TRUE, disable the <b>SaveButton</b> click, default is "false" */
  isDisable: PropTypes.bool,

  /** Reference an Form with his ID */
  formId: PropTypes.number,

  /** If TRUE, display an <b>Spinner</b> inside <b>SaveButton</b>, default is "false" */
  isLoading: PropTypes.bool,

  /** Control the type of <b>SaveButton</b>. */
  type: PropTypes.string,

  /** Remove the <b>CancelButton</b> */
  disableCancelButton: PropTypes.bool,

  /** Remove the <b>ConfirmButton</b> */
  disableConfirmButton: PropTypes.bool,

  /** Define the children inside <b>SaveButton</b>. Default is "Salvar" */
  saveButton: PropTypes.string,

  /** Define the children inside <b>CancelButton</b>. Default is "Cancelar" */
  cancelButton: PropTypes.string,

  /** This props render an element in the middle of the <b>CancelButton</b> and <b>SaveButon</b> */
  middleButtonComponent: PropTypes.element,

  confirmButtonName: PropTypes.string,

  cancelButtonName: PropTypes.string,

  /** Define the confir button variant. see all variant in <b>Atoms?Button</b>. */
  confirmButtonVariant: buttonPropTypes.variant,
  /** Define the confir button variant. see all variant in <b>Atoms?Button</b>. */
  cancelButtonVariant: buttonPropTypes.variant,
  /** If <b>true</b> add an one more click to confirm button onClick be trigerred */
  confirmSecurity: PropTypes.bool,
};

const footerButtonsDefaultProps = {
  onClick: () => {},
  onCancel: () => {},
  isDisable: false,
  formId: null,
  isLoading: false,
  type: "submit",
  disableCancelButton: false,
  disableConfirmButton: false,
  saveButton: null,
  cancelButton: null,
  middleButtonComponent: null,
  cancelButtonVariant: "text",
  confirmButtonName: "footer-confirm-button",
  cancelButtonName: "footer-cancel-button",
  confirmButtonVariant: buttonDefaultProps.variant,
  confirmSecurity: false,
};

/** Group of 2 <b>Button</b>, commonly used in CMS inside Form. */
function FooterButtons({
  onClick,
  onCancel,
  isDisable,
  formId,
  isLoading,
  disableCancelButton,
  disableConfirmButton,
  type,
  saveButton,
  cancelButton,
  middleButtonComponent,
  confirmButtonName,
  cancelButtonName,
  confirmButtonVariant,
  cancelButtonVariant,
  confirmSecurity,
  ...rest
}) {
  //* STATES
  const [confirmDelete, setConfirmDelete] = React.useState(false);

  const renderConfirmButton = () => {
    if (confirmDelete)
      return (
        <div style={{ display: "flex", alignItems: "center" }}>
          <Icon iconName="info" style={{ marginRight: 8 }} />
          <Trans translationKey="common.confirm">Confirmar</Trans>
        </div>
      );

    return saveButton || <Trans translationKey="common.save">Salvar</Trans>;
  };

  const handleOnConfirm = () => {
    if (confirmSecurity) {
      if (confirmDelete) onClick();
      else setConfirmDelete(true);
    } else {
      onClick();
    }
  };

  return (
    <Box display="flex" alignItems="center" justifyContent="flex-end" {...rest}>
      {!disableCancelButton && (
        <Button
          name={cancelButtonName}
          size="small"
          variant={cancelButtonVariant}
          onClick={e => stopPropagation(e, onCancel)}
        >
          {cancelButton || (
            <Trans translationKey="common.cancel">Cancelar</Trans>
          )}
        </Button>
      )}

      {middleButtonComponent}

      {!disableConfirmButton && (
        <Button
          name={confirmButtonName}
          disableElevation
          loading={isLoading}
          onClick={e => stopPropagation(e, handleOnConfirm)}
          disabled={isDisable}
          form={formId}
          type={type}
          variant={confirmButtonVariant}
          size="small"
          style={{ marginLeft: 8 }}
          {...saveButton}
        >
          {renderConfirmButton()}
        </Button>
      )}
    </Box>
  );
}

FooterButtons.propTypes = footerButtonsPropTypes;

FooterButtons.defaultProps = footerButtonsDefaultProps;

export { FooterButtons, footerButtonsPropTypes, footerButtonsDefaultProps };
