import React from "react";
import { useParams } from "react-router-dom";

import {
  openAdminDownloadMaterials,
  openCreateDownloadMaterials,
} from "~/shared/routes/navigation";
import { useDownloadMaterial } from "../../hooks";
import { isDataValid } from "~/shared/utils/validations";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { Spinner } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

import { Container, DownloadMaterialForm } from "./styles";

function EditDownloadMaterial() {
  const { showMaterial, material, isLoading } = useDownloadMaterial();

  const { materialEditId } = useParams();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  const { translate } = useTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(materialEditId)) {
      showMaterial(materialEditId);
    }

    return () => {
      isMounted = false;
    };
  }, [materialEditId, showMaterial]);

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.downloadMaterials.downloadableMaterials"),
        function: openAdminDownloadMaterials,
      },
      {
        id: 2,
        name: translate("entities.downloadMaterials.editMaterial"),
        function: openCreateDownloadMaterials,
      },
    ]);
    setEditMode(true);

    return () => {
      setEditMode(false);
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return isLoading ? (
    <Spinner centered />
  ) : (
    <Container>
      <DownloadMaterialForm material={material} editMode />
    </Container>
  );
}

export default EditDownloadMaterial;
