import api from "~/shared/services/api";

export const listPosters = ({ search = "", page = 1 }) =>
  Promise.resolve(
    api.get("/admin/poster", {
      params: {
        search,
        page,
      },
    })
  );

export const createPoster = ({ posterData }) =>
  Promise.resolve(api.post("/admin/poster", posterData));

export const updatePoster = ({ posterData, posterId }) =>
  Promise.resolve(api.put(`/admin/poster/${posterId}`, posterData));

export const deletePoster = ({ posterId }) =>
  Promise.resolve(api.delete(`/admin/poster/${posterId}`));

export const showPoster = ({ posterId }) =>
  Promise.resolve(api.get(`/admin/poster/${posterId}`));
