import produce from "immer";

//* CUSTOM IMPORTS
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
  },
};

export default function roomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case types.NETWORK_ROOMS.RECEIVE: {
        const { networkRooms } = action.payload;

        draft.list.data = networkRooms;

        draft.list.isLoaded = true;

        break;
      }

      //* SIGN OUT
      case "@auth/SIGN_OUT":
      case types.NETWORK_ROOMS.CLEAR: {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }
      default:
    }
  });
}
