import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Text } from "./styles";

const tipPropTypes = {
  /** Control the visibility of the component */
  show: PropTypes.bool,

  /** Text to be rendered inside the <b>Tip</b> */
  content: PropTypes.string,

  /** Opacity of the component */
  opacity: PropTypes.number,

  /** Direction that the component will apear <b>PARENT COMPONENT MUST BE RELATIVE</b> */
  direction: PropTypes.oneOf(["right", "top", "botton", "left"]),

  /** Defines the min widght of the Tip card */
  size: PropTypes.number,
};

const tipDefaultProps = {
  show: false,
  content: "",
  opacity: 0.5,
  direction: "top",
  size: 0,
};

const Tip = ({ content, opacity, direction, size, ...rest }) => {
  return (
    <Container
      opacity={opacity}
      direction={direction}
      size={`${size}px`}
      {...rest}
    >
      <Text className="small bold">{content}</Text>
    </Container>
  );
};

Tip.propTypes = tipPropTypes;
Tip.defaultProps = tipDefaultProps;

export { Tip, tipPropTypes, tipDefaultProps };
