import api, { socketApi } from "~/shared/services/api";

export const blockAllUserChats = ({ userId }) =>
  Promise.resolve(
    socketApi.post(`/admin/block`, {
      user_id: userId,
      type: "chat_all",
    })
  );

export const unBlockUserChat = ({ userId }) =>
  Promise.resolve(api.delete(`/admin/block/${userId}`));
