import React from "react";
import { MdPeople } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import { receiveNetworkRooms } from "~/modules/signed/modules/client/store/networkRooms/actions";

//* APIs
import { listNetworkRooms } from "~/modules/signed/modules/client/apis/networkRooms";
import { createActiveLog } from "~/modules/signed/modules/client/apis/activeLog";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { isListValid } from "~/shared/utils/validations";
import { openMarketRoom } from "~/shared/routes/navigation";
import { NetworkRoomCell } from "~/shared/components/organisms/cells/NetworkRoomCell";
import NetworkCellLoader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { CellList, Empty } from "~/shared/components/molecules";
import { Typography, Trans } from "~/shared/components/atoms";
import { Container, Header, SearchBar } from "./styles";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";
import { ACTIVE_LOG_TYPES } from "~/shared/utils/types";

function NetworkRoomList() {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: networkRooms, isLoaded } = useSelector(
    state => state.networkRooms.list
  );

  //* STATES
  const [search, setSearch] = React.useState("");
  const [isSearching, setIsSearching] = React.useState(false);

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const list = async () => {
      await listNetworkRooms({ search: "" }).then(response =>
        dispatch(receiveNetworkRooms({ networkRooms: response.data }))
      );
    };

    list();
  }, [dispatch]);

  const handleSearch = async s => {
    setIsSearching(true);

    await listNetworkRooms({ search: s }).then(response =>
      dispatch(receiveNetworkRooms({ networkRooms: response.data }))
    );

    setIsSearching(false);
  };

  const handleNetworkRoomClick = (room, videoCallUrl) => {
    createActiveLog({
      action: ACTIVE_LOG_TYPES.NETWORK_ROOM_CLICK,
      refId: room.id,
    });
    window.open(videoCallUrl);
  };

  return (
    <Container>
      <Header>
        <Typography className="title bold accent">
          <Trans translationKey="entities.networkRooms.plural">
            Salas de Networking
          </Trans>
        </Typography>
        <SearchBar
          placeholder={translate("entities.networkRooms.search")}
          onChange={value => {
            setSearch(value);
            handleSearch(value);
          }}
          isSearching={isSearching}
        />
      </Header>

      <CellList
        isLoaded={!isSearching && isLoaded}
        data={networkRooms}
        renderCell={networkRoom => {
          const { room, video_call_url: videoCallUrl } = networkRoom;

          return (
            <NetworkRoomCell
              title={room.title}
              description={room.description}
              image={room.logo}
              tags={room.tags}
              id={room.id}
              onClick={() => handleNetworkRoomClick(room, videoCallUrl)}
              customButton={
                <BookmarkButton
                  isSelected={room.bookmark}
                  apiType="rooms"
                  apiId={room.id}
                  variant="icon"
                />
              }
              bookmark={room.bookmark}
              bottomTags={
                isListValid(room.partners) &&
                room.partners.map(partner => ({
                  name: partner.title,
                  onClick: () => openMarketRoom(partner.id, partner.title),
                  hoverable: true,
                }))
              }
              isBlocked={!room.allow_access}
            />
          );
        }}
        renderCellLoader={() => <NetworkCellLoader />}
        gap={20}
        emptyComponent={
          <Empty
            variant={search && "search"}
            search={search}
            icon={<MdPeople />}
            message={
              <Trans translationKey="entities.networkRooms.empty.message">
                Sem Salas de Networking
              </Trans>
            }
            description={
              <Trans translationKey="entities.networkRooms.empty.description">
                Ainda não há nenhuma sala de networking cadastrada
              </Trans>
            }
          />
        }
      />
    </Container>
  );
}

export default NetworkRoomList;
