import React from "react";

//* PROVIDER
import { SponsorScheduleProvider, useSponsorSchedule } from "./hooks";

//* CUSTOM IMPORTS
import { Spinner, Typography } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useCMS } from "../../../../hooks";
import { SponsorScheduleForm } from "./components/ScheduleForm";
import { SponsorSchedulePreview } from "./components/SchedulePreview";
import { Container, LeftContainer, RightContainer } from "./styles";

const Content = () => {
  const { translate } = useTranslation();
  const { showFooterButtons, closeFooterButtons } = useCMS();
  const { isFormLoading, isSaving } = useSponsorSchedule();

  //* FUNCTIONS
  React.useEffect(() => {
    showFooterButtons({
      formId: "sponsor-schedule-form",
      isLoading: isSaving,
    });
  }, [isSaving, showFooterButtons]);

  React.useEffect(() => {
    return () => {
      closeFooterButtons();
    };
  }, [closeFooterButtons]);

  return isFormLoading ? (
    <Spinner centered size="large" />
  ) : (
    <Container>
      <LeftContainer>
        <SponsorScheduleForm id="sponsor-schedule-form" />
      </LeftContainer>

      <RightContainer>
        <Typography
          className="subTitle bold accent"
          style={{ marginBottom: 10 }}
        >
          {translate("common.preVisualization")}
        </Typography>

        <SponsorSchedulePreview />
      </RightContainer>
    </Container>
  );
};

export const SponsorSchedule = () => (
  <SponsorScheduleProvider>
    <Content />
  </SponsorScheduleProvider>
);

export default SponsorSchedule;
