import React from "react";
import PropTypes from "prop-types";

//* APIs
import { listUserMeetings, deleteUserMeeting } from "../../../apis/meetings";

//* CUSTOM IMPORTS
import { meetingTypes } from "./store/meetings/types";
import {
  meetingsReducer,
  MEETINGS_INITIAL_DATA,
} from "./store/meetings/reducer";

const MyScheduleContext = React.createContext(null);

const MyScheduleProvider = ({ children }) => {
  const [meetings, dispatch] = React.useReducer(
    meetingsReducer,
    MEETINGS_INITIAL_DATA
  );

  //* FUNCTIONS
  const listMyMeetings = React.useCallback(search => {
    dispatch({ type: meetingTypes.LIST.REQUEST });

    listUserMeetings({ page: 1, search }).then(({ data }) =>
      dispatch({ type: meetingTypes.LIST.SUCCESS, payload: data })
    );
  }, []);

  /** This function will paginate meetings states and deal with all sideEffects */
  const paginateMyMeetings = React.useCallback((page, search) => {
    dispatch({ type: meetingTypes.PAGINATE.REQUEST });

    listUserMeetings({ page, search }).then(({ data }) =>
      dispatch({ type: meetingTypes.PAGINATE.SUCCESS, payload: data })
    );
  }, []);

  /** This function will call an APi to cancel the meetingm, then deals with sideEffect */
  const cancelMyMeeting = (scheduledMeetingId, startDate) => {
    dispatch({
      type: meetingTypes.DELETE.REQUEST,
      payload: { scheduledMeetingId, startDate },
    });

    deleteUserMeeting({ scheduledMeetingId }).then(() => {
      dispatch({
        type: meetingTypes.DELETE.SUCCESS,
        payload: { scheduledMeetingId, startDate },
      });
    });
  };

  /** This function only add an newMetting in meetings.data list. */
  const addMeeting = React.useCallback(newMeeting => {
    dispatch({ type: meetingTypes.ADD.SUCCESS, payload: { newMeeting } });
  }, []);

  return (
    <MyScheduleContext.Provider
      value={{
        meetings,

        //* Functions
        addMeeting,
        listMyMeetings,
        cancelMyMeeting,
        paginateMyMeetings,
      }}
    >
      {children}
    </MyScheduleContext.Provider>
  );
};

MyScheduleProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useMySchedule = () => {
  const context = React.useContext(MyScheduleContext);

  if (!context)
    throw new Error("useMySchedule must be within MyScheduleProvider");

  return context;
};

export { useMySchedule, MyScheduleProvider };
