import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { CMSTranslationMenu } from "./components/CMSTranslationMenu";
import { pushState } from "~/shared/services/history";
import { SideMenu, sideMenuPropTypes } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { clientPaths } from "~/shared/routes/paths";
import { Icon } from "~/shared/components/atoms";
import { Header } from "./styles";

const cmsSideMenuPropTypes = {
  data: sideMenuPropTypes.topNavigation,
  headerComponent: PropTypes.element,
};

const cmsSideMenuDefaultProps = {
  data: [],
  headerComponent: null,
};

const CMSSideMenu = ({ data, headerComponent }) => {
  const { translate } = useTranslation();

  //* REFS
  const scrollRef = React.useRef(null);

  //* STATES
  const [selectedItem, setSelectedItem] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    const { pathname } = window.location;

    let allItems = [];

    data.forEach(item => {
      allItems = [...allItems, ...item.data];
    });

    //* *Check if the pathname contains an path of some module item, if contains
    //* * set the active item as the respective module item id */
    allItems.forEach((item, index) => {
      const { id, path } = item;

      if (String(pathname).includes(path)) {
        setSelectedItem(id);

        if (scrollRef.current) scrollRef.current.scrollTop = index * 52;
      }
    });
  }, [data]);

  const handleOnItemClick = item => {
    setSelectedItem(item.id);
    pushState(item.path);
  };

  return (
    <SideMenu
      scrollRef={ref => {
        scrollRef.current = ref;
      }}
      headerComponent={
        <Header>
          <CMSTranslationMenu />

          {headerComponent}
        </Header>
      }
      onItemClick={handleOnItemClick}
      topNavigation={data}
      selectedItem={selectedItem}
      bottomNavigation={[
        {
          id: 100,
          content: translate("common.backToPlatform"),
          path: clientPaths.home.broadcastRooms.index,
          icon: <Icon iconName="exit" />,
        },
      ]}
      style={{ width: 300, height: "100vh" }}
    />
  );
};

CMSSideMenu.propTypes = cmsSideMenuPropTypes;

CMSSideMenu.defaultProps = cmsSideMenuDefaultProps;

export { CMSSideMenu };
