import React from "react";
import { useLocation, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import { showPartner } from "~/modules/signed/modules/cms/modules/admin/store/partners/actions";

// CUSTOM IMPORTS
import MarketRoomForm from "../components/MarketRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminMarketRooms } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { Spinner } from "~/shared/components/atoms";

function EditMarketRoom() {
  const location = useLocation();
  const { marketRoomName, partnerId } = useParams();

  // REDUCER
  const dispatch = useDispatch();
  const partnerReducer = useSelector(state => state.adminPartners.object);
  const { data: partner, isLoaded } = partnerReducer;

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: process.env.REACT_APP_MARKET_ROOMS_NAME,
        function: openAdminMarketRooms,
      },
      {
        id: 2,
        name: marketRoomName,
        function: openAdminMarketRooms,
      },
      {
        id: 3,
        name: "Editar Estande Virtual",
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [marketRoomName, setBreadCrumbs, setEditMode]);

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(partnerId))
      dispatch(showPartner(partnerId));

    return () => {
      isMounted = false;
    };
  }, [dispatch, partnerId]);

  return isLoaded ? (
    <MarketRoomForm
      marketRoomId={isDataValid(location.state) && location.state.marketRoomId}
      partner={partner}
    />
  ) : (
    <Spinner centered />
  );
}

export default EditMarketRoom;
