import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import Loader from "../../molecules/cellLoaders/CheckListCellLoader";
import {
  CheckListItem,
  CellList,
  Empty,
  emptyPropTypes,
  cellListPropTypes,
  checkListItemPropTypes,
  checkListItemDefaultProps,
} from "../../molecules";
import { ScrollableCard } from "./styles";

const checkListPropTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  isLoading: PropTypes.bool,

  /** Callback that is trigered when user clicks into <b>EditButton</b>,
   * returns the clicked object.
   *
   * Example: (obj) => {}
   */
  onEdit: PropTypes.func,

  /** Callback that is trigered when user clicks into <b>DeleteButton</b>,
   * returns the clicked object.
   *
   * Example: (obj) => {}
   */
  onDelete: PropTypes.func,

  /** Callback that is trigered when user clicks into <b>Checkbox</b>,
   * returns the clicked object.
   *
   * Example: (obj) => {}
   */
  onCheck: PropTypes.func,

  /** Callback that is trigered when user clicks into <b>CheckListItem</b>,
   * returns the clicked object.
   *
   * Example: (obj) => {}
   */
  onClick: PropTypes.func,

  /** Control the <b>Empty</b> component props, to see all props see
   * <b>Molecules/Empty</b>. If this props has data render an <b>Empty</b>
   * component when <b>data</b> is empty
   */
  empty: PropTypes.shape(emptyPropTypes),

  cellList: PropTypes.shape(cellListPropTypes),

  variant: checkListItemPropTypes.variant,

  disableCard: PropTypes.bool,
};

const checkListDefaultProps = {
  isLoading: false,
  onEdit: null,
  onDelete: null,
  onCheck: () => {},
  onClick: () => {},
  empty: null,
  cellList: null,
  variant: checkListItemDefaultProps.variant,
  disableCard: false,
};

/** This component use <b>ScrollableCard</b>, see all props into <b>Molecules/ScrollableCard</b> */
const CheckList = ({
  isLoading,
  data,
  onEdit,
  variant,
  onDelete,
  onCheck,
  empty,
  cellList,
  onClick,
  disableCard,
  ...rest
}) => (
  <ScrollableCard {...rest} disableCard={disableCard}>
    <CellList
      {...cellList}
      orientation="column"
      isLoaded={!isLoading}
      data={data}
      renderCell={obj => (
        <CheckListItem
          {...obj}
          onClick={() => onClick(obj)}
          variant={variant}
          title={obj.title}
          checkbox={{
            onClick: () => onCheck(obj),
            checked: obj.isChecked,
          }}
          onEdit={onEdit ? () => onEdit(obj) : null}
          onDelete={onDelete ? () => onDelete(obj) : null}
        />
      )}
      renderCellLoader={() => <Loader style={{ margin: 10, width: "100%" }} />}
      emptyComponent={empty && <Empty {...empty} style={{ padding: 20 }} />}
    />
  </ScrollableCard>
);

CheckList.propTypes = checkListPropTypes;

CheckList.defaultProps = checkListDefaultProps;

export { CheckList, checkListPropTypes, checkListDefaultProps };
