import React from "react";
import PropTypes from "prop-types";
import { MdVideocamOff } from "react-icons/md";

// CUSTOM IMPORTS
import { isDataValid } from "../../../utils/validations";
import {
  Container,
  Card,
  VideoUrlInput,
  VideoTypeInput,
  Title,
  NoPlayer,
  PlyrPlayer,
  IFrame,
} from "./styles";
import { debounceEvent } from "../../../utils/forms";

const playerInputPropTypes = {
  name: PropTypes.string.isRequired,
  typeName: PropTypes.string,
  title: PropTypes.string,
  className: PropTypes.string,
  src: PropTypes.string,
  type: PropTypes.string,
  onChange: PropTypes.func,
  disableVideoTypeValidation: PropTypes.bool,
  height: PropTypes.string,
};

const playerInputDefaultProps = {
  typeName: "type",
  title: "",
  className: "",
  src: "",
  type: "youtube",
  disableVideoTypeValidation: false,
  onChange: () => {},
  height: "100%",
};

function PlayerInput({
  className,
  name,
  typeName,
  title,
  src,
  type,
  onChange,
  disableVideoTypeValidation,
  height,
  ...props
}) {
  // STATES
  const [videoSrc, setVideoSrc] = React.useState(src);
  const [videoType, setVideoType] = React.useState(type);

  // FUNCTIONS
  function validateVideoSrc(videoSource) {
    if (String(videoSource).match("http")) return true;

    return false;
  }

  function handleInputChange() {
    const playerInput = document.getElementById(name);

    if (isDataValid(playerInput)) {
      const inputValue = playerInput.value;

      // Clear src value, because Plyr player need to be re-renderer to change video source
      setVideoSrc("");
      setVideoSrc(inputValue);

      if (disableVideoTypeValidation) return;

      if (validateVideoSrc(inputValue)) {
        // If video source has vimeo set type as vimeo, else change to youtube
        if (
          String(inputValue).match("vimeo.com") ||
          String(inputValue).match("boxcast.tv")
        ) {
          setVideoType("vimeo");

          onChange(inputValue, "vimeo");
        } else {
          setVideoType("youtube");

          onChange(inputValue, "youtube");
        }
      }
    }
  }

  return (
    <Container className={className}>
      {title && <Title>{title}</Title>}

      <Card height={height}>
        {validateVideoSrc(videoSrc) ? (
          <>
            {videoType === "vimeo" ? (
              <div id="player-container">
                <IFrame
                  id="iframe-fragment"
                  allowfullscreen
                  frameborder="0"
                  allow="accelerometer; autoplay; encrypted-media; fullscreen; microphone; camera; gyroscope; picture-in-picture"
                  src={videoSrc}
                />
              </div>
            ) : (
              <PlyrPlayer
                url={videoSrc}
                type={videoType || "youtube"}
                className="plyr-player"
              />
            )}
          </>
        ) : (
          <NoPlayer>
            <MdVideocamOff />
          </NoPlayer>
        )}

        <VideoTypeInput name={typeName} value={videoType} />

        <VideoUrlInput
          id={name}
          name={name}
          {...props}
          onChange={debounceEvent(handleInputChange, 1000)}
        />
      </Card>
    </Container>
  );
}

PlayerInput.propTypes = playerInputPropTypes;

PlayerInput.defaultProps = playerInputDefaultProps;

export { PlayerInput, playerInputPropTypes, playerInputDefaultProps };
