import api from "~/shared/services/api";

export const apiListStickyNotes = ({ page = 1 }) =>
  api.get("/sticky_note", { params: { page } });

export const apiCreateStickyNotes = ({ data }) =>
  api.post("/sticky_note", data);

export const apiDeleteStickyNote = ({ id }) => api.delete(`/sticky_note/${id}`);

export const likeStyckyNote = ({ id }) =>
  api.post("/sticky_note/like", { sticky_note_id: id });

export const dislikeStyckyNote = ({ id }) =>
  api.delete("/sticky_note/like", {
    params: {
      sticky_note: id,
    },
  });

//* STICKY NOTE COMMENTS
export const listStickyNoteComments = ({ id, page }) =>
  api.get(`/sticky_note/comment`, { params: { sticky_note: id, page } });

export const createStickyNoteComments = ({ id, comment, commentReplyId }) =>
  api.post(`/sticky_note/comment`, {
    comment,
    sticky_note_id: id,
    comment_reply_id: commentReplyId,
  });

export const deleteStickyNoteComment = ({ id }) =>
  api.delete(`/sticky_note/comment/${id}`);

export const likeStickyNoteComment = ({ id }) =>
  api.post("sticky_note/comment/reaction", {
    comment_id: id,
    emoji_type: ":like:",
  });

export const dislikeStickyNoteComment = ({ id }) =>
  api.delete(`sticky_note/comment/reaction`, {
    params: {
      comment: id,
    },
  });
