import styled, { css } from "styled-components";
import PerfectScroll from "react-perfect-scrollbar";

//* CUSTOM IMPORTS
import { fadeIn } from "../../../styles/animations";
import { Tooltip, Button, IconButton as IconButtonC, Card } from "../../atoms";

export const Container = styled.div`
  ${props =>
    props.isFilled ||
    (props.isOptionsOpened &&
      css`
        #dropdown-title {
          color: ${props.theme.colors.accent};
        }
      `)}
`;

export const DropdownContainer = styled.div`
  position: relative;
  min-height: 48px;
  display: flex;
  align-items: center;
  width: 100%;
  background: ${props => props.theme.colors.surface};
  border-radius: 4px;
  border: none;
  box-shadow: 0 2px 5px ${props => props.theme.colors.shadow};
  z-index: 1;

  ${props =>
    props.optionsOn &&
    css`
      border-radius: 4px 4px 0 0;
      border-bottom: 1px solid ${props.theme.colors.shadow};

      .dropdown-icon {
        transform: rotate(180deg);
      }
    `}

  ${props =>
    props.isErrored &&
    css`
      border: 2px solid ${props.theme.colors.redWrong};
    `}
`;

export const Title = styled.h4`
  margin: 0 0 10px 0;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: ${props => props.theme.colors.normal};
  width: 100%;
  text-align: left;

  span {
    margin-left: 2px;
    font-weight: bold;
    font-size: 14px;
    color: ${props => props.theme.colors.redWrong};
  }
`;

export const InputArea = styled.div`
  flex: 1;
`;

export const Input = styled.input`
  border: none;
  background: none;
  width: 100%;
  padding: 0 16px;
  color: ${props => props.theme.colors.strong};
`;

export const IconButton = styled(IconButtonC)`
  margin-left: auto;
`;

export const Options = styled(Card)`
  position: absolute;
  top: 48px;
  margin-top: 8px;

  max-height: 200px;
  padding: 0px;
  display: ${props => (props.show ? "flex" : "none")};
  flex-direction: column;
  width: 100%;

  animation: ${fadeIn} 0.3s ease-in;
`;

export const Scroll = styled(PerfectScroll)`
  max-height: 200px;
  padding: 8px;
`;

export const Option = styled(Button)`
  .MuiButton-label {
    display: flex;
    justify-content: flex-start;
  }
`;

export const Error = styled(Tooltip)`
  margin-right: 16px;
  cursor: pointer;

  svg {
    height: 20px;
    width: 20px;
    color: ${props => props.theme.colors.redWrong};
  }
`;
