import styled, { keyframes } from "styled-components";

//* CUSTOM IMPORTS
import { medias } from "~/shared/styles";
import { topIn } from "~/shared/styles/animations";
import { IndividualChat as IndividualChatC } from "~/shared/components/organisms";

const leftAnimation = keyframes`
  from{
    left: -100%;
    opacity: 0;
  }

  to{
    left: 0%;
    opacity: 1;
  }
`;

const leftAnimation2 = keyframes`
  from{
    left: -100%;

  }

  to{
    left: 0%;

  }
`;

export const IndividualChat = styled(IndividualChatC)`
  position: relative;
  height: 452px;
  width: 400px;

  animation: ${props => (props.firstOne ? leftAnimation : leftAnimation2)} 0.5s
    ease;

  @media (max-width: ${medias.mobile}) {
    width: 100vw;
    height: calc(100vh - 140px);
    animation: ${topIn} 0.3s ease;
  }
`;
