import React from "react";
import { useParams } from "react-router-dom";

//* CUSTOM IMPORTS
import { ProfileHeader } from "./components/ProfileHeader";
import { ProfileBody } from "./components/ProfileBody";
import { Spinner } from "~/shared/components/atoms";
import { Container } from "./styles";
import { useProfile } from "./hooks";
import { useUser } from "../../hooks";
import { useProfileGamification } from "./hooks/gamification";
import { useBookmarkProfile } from "./hooks/bookmarks";
import { useSigned } from "~/modules/signed/hooks";

function Profile() {
  const { userId } = useParams();
  const { setHeaderTitle } = useUser();
  const { getProfilePoints } = useProfileGamification();
  const { isMyUserId, user } = useSigned();
  const { openProfile, selectedUser, isUserLoaded } = useProfile();
  const { clearBookmarks } = useBookmarkProfile();

  const itsMe = !userId || isMyUserId(parseInt(userId, 10));

  //* FUNCTIONS
  React.useEffect(() => {
    clearBookmarks();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userId]);

  React.useEffect(() => {
    if (userId) getProfilePoints(userId);
  }, [getProfilePoints, userId]);

  React.useEffect(() => {
    openProfile(!itsMe ? userId : user.id);
  }, [itsMe, openProfile, user.id, userId]);

  React.useEffect(() => {
    if (selectedUser) setHeaderTitle(selectedUser.name);

    return () => {
      setHeaderTitle(null);
    };
  }, [selectedUser, setHeaderTitle]);

  return isUserLoaded ? (
    <Container>
      <ProfileHeader user={selectedUser} itsMe={itsMe} />
      <ProfileBody user={selectedUser} itsMe={itsMe} />
    </Container>
  ) : (
    <Spinner centered />
  );
}

export default Profile;
