import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getUsersRegisterExport } from "~/modules/signed/modules/cms/modules/admin/apis/exports/users";
import { getUsersReportCount } from "~/modules/signed/modules/cms/modules/admin/apis/users";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { DashboardReportCard } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function UsersCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: usersReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getUsersReportCount({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardReportCard
      downloadButtonData={{
        callDownloadSheetsApi: () =>
          getUsersRegisterExport({
            endDate: dateInterval.endDate,
            startDate: dateInterval.startDate,
            timezone,
          }),
        sheetsName: translate("entities.reports.users"),
      }}
      isLoaded={isLoaded}
      title={translate("entities.users.newUsers")}
      dotColor="#2B8DFD"
      reportItems={[
        {
          id: 1,
          counter: isLoaded && usersReport.total_users,
          description: translate("entities.users.totalNewUsers"),
        },
        {
          id: 2,
          counter: isLoaded && usersReport.total_registration_completed,
          description: translate("common.completedRegistration"),
        },
      ]}
    />
  );
}

UsersCard.propTypes = {
  dateInterval: PropTypes.shape({
    startDate: PropTypes.any,
    endDate: PropTypes.any,
  }).isRequired,
  timezone: PropTypes.number.isRequired,
};

export default UsersCard;
