import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import { Route, Redirect, useLocation } from "react-router-dom";

//* PAGES
import NotAllowed from "~/modules/signed/modules/cms/modules/admin/pages/NotAllowed";

//* ACTIONS
import { signInApp } from "~/modules/auth/store/auth/actions";

//* CUSTOM IMPORTS
import { CLIENT_HEADER_VARIANTS } from "~/modules/signed/modules/client/hooks";
import { isDataValid } from "~/shared/utils/validations";
import { isStringEmpty } from "~/shared/utils/strings";
import { AuthProvider } from "~/modules/auth/hooks";
import { SignedProvider } from "~/modules/signed/hooks";
import { isRouteAllowed } from "./accessControl";
import { USER_TYPES } from "../utils/user";

/** This component controls the business rules of all the application's routes. */
export default function RouteWrapper({
  component: Component,
  provider: Provider,
  providerProps,
  isPrivate,
  isAdmin,
  title,
  layer,
  headerOff,
  headerVariant,
  returnPath,
  disableLayer,
  isHibrid,
  ...rest
}) {
  const dispatch = useDispatch();
  const location = useLocation();
  const { signed } = useSelector(state => state.auth);
  const {
    registration_completed: registrationCompleted,
    access_type: accessType,
    partners_ids: partnersIds,
  } = useSelector(state => state.user.profile.data);

  // FUNCTIONS
  React.useEffect(() => {
    if (isDataValid(location)) {
      // Valid if has some query params
      if (isDataValid(location.search) && !isStringEmpty(location.search)) {
        const queryString = location.search;
        const pathName = location.pathname;

        const urlParams = new URLSearchParams(queryString);

        const email = urlParams.get("email");
        const token = urlParams.get("token");

        if (isDataValid(email) && isDataValid(token))
          dispatch(signInApp({ email, token, pathName }));
      }
    }
  }, [dispatch, location]);

  /** Check the user route permission based on his accessType */
  const routeAllowed = React.useMemo(() => {
    let userAccessType = accessType;

    /** Transform an sponsor user in common user if he do not has an linked partner. */
    if (accessType === USER_TYPES.SPONSOR.value) {
      if (partnersIds)
        if (!partnersIds.length) userAccessType = USER_TYPES.COMMOM_USER.value;
    }

    return isRouteAllowed(
      userAccessType || USER_TYPES.COMMOM_USER.value,
      location.pathname
    );
  }, [accessType, location.pathname, partnersIds]);

  /** If user has no permission to access the route and is signed render the
   * NotAllowed page.
   */
  if (!routeAllowed && signed) {
    return <Route component={NotAllowed} />;
  }

  if (!signed && isPrivate) {
    return <Redirect to="/" />;
  }

  if (signed) {
    if (isPrivate) {
      const isInCompleteSignUp = location.pathname === "/register/complete";

      if (!registrationCompleted && !isInCompleteSignUp)
        return <Redirect to="/register/complete" />;
    } else {
      if (!registrationCompleted) return <Redirect to="/register/complete" />;

      if (!isHibrid) return <Redirect to="/home/broadcast-rooms" />;
    }
  }

  let Layer = null;

  // Case has a forced layer then use it
  if (layer) {
    Layer = layer;
  } else if (disableLayer) {
    Layer = Fragment;
  } else {
    Layer = signed ? SignedProvider : AuthProvider;
  }

  return (
    <Route
      {...rest}
      render={({ ...props }) => (
        <Layer
          title={title}
          headerOff={headerOff}
          headerVariant={headerVariant}
          isAdmin={isAdmin}
          returnPath={returnPath}
        >
          {Provider ? (
            <Provider {...providerProps}>
              <Component {...props} />
            </Provider>
          ) : (
            <Component {...props} />
          )}
        </Layer>
      )}
    />
  );
}

RouteWrapper.propTypes = {
  isPrivate: PropTypes.bool,
  isAdmin: PropTypes.bool,
  title: PropTypes.string,
  layer: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
  component: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
  headerOff: PropTypes.bool,
  returnPath: PropTypes.bool,
  provider: PropTypes.any,
  providerProps: PropTypes.object,
  headerVariant: PropTypes.oneOf([
    CLIENT_HEADER_VARIANTS.DEFAULT,
    CLIENT_HEADER_VARIANTS.RETURN,
  ]),
  /** Disable the public routes style layer */
  disableLayer: PropTypes.bool,
  /** If true the URL will be both public and private */
  isHibrid: PropTypes.bool,
};

RouteWrapper.defaultProps = {
  isPrivate: false,
  isAdmin: false,
  title: null,
  layer: null,
  headerOff: false,
  returnPath: false,
  provider: null,
  providerProps: null,
  headerVariant: CLIENT_HEADER_VARIANTS.RETURN,
  disableLayer: false,
  isHibrid: false,
};
