import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography, LinkButton, Box } from "../../atoms";
import { CellList } from "../CellList";

const breadCrumbsPropTypes = {
  /** Array of object, { id: number, name: string, function: func } */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      function: PropTypes.func,
    })
  ).isRequired,

  /** The index of the crumb to be selected */
  selectedCrumb: PropTypes.number,

  /** Callback that is triggered when user click into some Crumb,
   * return ({id, name, function }) => {}
   */
  onChange: PropTypes.func,

  /** Define component styles */
  style: PropTypes.object,
};

const breadCrumbsDefaultProps = {
  style: null,
  onChange: () => {},
  selectedCrumb: null,
};

/** BreadCrumbs is commonly used for display navigation between pages  */
const BreadCrumbs = ({ data, style, selectedCrumb, onChange }) => {
  //* FUNCTIONS
  const handleCrumbClick = (crumb, index, callback) => {
    onChange(crumb, index);

    if (callback) callback();
  };

  return (
    <CellList
      isLoaded
      containerStyle={style}
      data={data}
      renderCell={(crumb, index) => {
        const isTheLastCrumb = index === data.length - 1;
        const isActive = selectedCrumb === index;

        return (
          <Box display="flex" alignItems="center">
            <LinkButton
              style={{ fontSize: 16, fontWeight: !isActive && "normal" }}
              variant={isActive ? "accent" : "opaque"}
              onClick={() => handleCrumbClick(crumb, index, crumb.function)}
            >
              {crumb.name}
            </LinkButton>

            {!isTheLastCrumb && (
              <Typography
                className="opaque subTitle"
                style={{ margin: "0 8px" }}
              >{`>`}</Typography>
            )}
          </Box>
        );
      }}
    />
  );
};

BreadCrumbs.propTypes = breadCrumbsPropTypes;

BreadCrumbs.defaultProps = breadCrumbsDefaultProps;

export { BreadCrumbs, breadCrumbsPropTypes, breadCrumbsDefaultProps };
