import React from "react";
import PropTypes from "prop-types";
import { shade } from "polished";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

const CardLoader = ({ isDeleting, width, height, variant, style, ...rest }) => {
  const { colors } = React.useContext(ThemeContext);
  return (
    <div
      {...rest}
      style={{ display: "flex", justifyContent: "center", ...style }}
    >
      <ContentLoader
        speed={2}
        viewBox={`0 0 ${width} ${height}`}
        backgroundColor={
          variant === "primary"
            ? shade(0.15, colors.primary)
            : colors.contentLoaderBackground
        }
        foregroundColor={
          variant === "primary"
            ? shade(0.1, colors.primary)
            : colors.contentLoaderForeground
        }
        {...rest}
      >
        <rect x="0" y="0" rx="8" ry="8" width={width} height={height} />
      </ContentLoader>

      {isDeleting && (
        <h4
          style={{
            margin: 0,
            fontWeight: "bold",
            marginTop: 4,
            color: colors.opaque,
            fontSize: 12,
          }}
        >
          Excluindo...
        </h4>
      )}
    </div>
  );
};

CardLoader.propTypes = {
  isDeleting: PropTypes.bool,
  width: PropTypes.number,
  height: PropTypes.number,
  variant: PropTypes.oneOf(["primary", "default"]),
  style: PropTypes.object,
};

CardLoader.defaultProps = {
  width: 300,
  height: 128,
  isDeleting: false,
  variant: "default",
  style: null,
};

export default CardLoader;
