import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { IFrameContainer } from "./styles";

const iFramePropTypes = {
  /** The iframe need an ID to be finded inside the DOM to prevent the
   * back button inside himself
   */
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,

  /** The URL of the iframe will render */
  src: PropTypes.string.isRequired,

  /** The Height in <b>px</b> for the IFrame. Default heigth is 400 */
  height: PropTypes.number,
};

const IFrameDefaultProps = {
  height: 400,
};

/** Just an simple IFrame with some styles */
const IFrame = ({ id, src, height, ...rest }) => {
  const frame = document.getElementById(id);

  if (frame) frame.contentWindow.history.back();

  return <IFrameContainer src={src} height={`${height}px`} {...rest} />;
};

IFrame.propTypes = iFramePropTypes;

IFrame.defaultProps = IFrameDefaultProps;

export { IFrame, iFramePropTypes, IFrameDefaultProps };
