import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  listAdminRegistrationFields,
  clearAdminRegistrationFields,
} from "~/modules/signed/modules/cms/modules/admin/store/registrationFields/actions";

// CUSTOM IMPORTS
import CellLoader from "~/shared/components/molecules/cellLoaders/FieldsetLoader";
import { ContentLoader, Container, Fieldset } from "./styles";
import { Trans } from "~/shared/components/atoms";
import { INPUT_TYPES } from "~/shared/utils/inputs";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function UserAttributeList({ user, isUserLoaded }) {
  const dispatch = useDispatch();
  const { data: registrationFields, isLoaded } = useSelector(
    state => state.adminRegistrationFields.list
  );

  // FUNCTIONS
  const { dTranslate } = useDynamicTranslation();

  React.useEffect(() => {
    dispatch(listAdminRegistrationFields());

    return () => {
      dispatch(clearAdminRegistrationFields());
    };
  }, [dispatch]);

  function setAttributeValue(refCode, isJson) {
    let attribute = null;

    if (user.attributes)
      Object.entries(user.attributes).forEach(([key, value]) => {
        if (refCode === key) {
          if (isJson) {
            attribute = value && value.value;
          } else {
            attribute = value;
          }
        }
      });

    return attribute === null ? (
      <small>
        <Trans translationKey="common.unavailable">Indisponível</Trans>
      </small>
    ) : (
      attribute
    );
  }

  function renderField(field) {
    if (
      field.ref_code !== "email" &&
      field.ref_code !== "name" &&
      field.ref_code !== "password"
    )
      return (
        <Fieldset key={field.ref_code}>
          <h4>{dTranslate(field.title)}</h4>

          <p>
            {setAttributeValue(
              field.ref_code,
              field.type === INPUT_TYPES.PHONE.value
            )}
          </p>
        </Fieldset>
      );

    return <></>;
  }

  return isLoaded && isUserLoaded ? (
    <Container>
      <Fieldset>
        <h4>Email</h4>

        <p>{user.email}</p>
      </Fieldset>

      {registrationFields.map(field => renderField(field))}
    </Container>
  ) : (
    <ContentLoader cellLoader={<CellLoader />} size={5} />
  );
}

UserAttributeList.propTypes = {
  user: PropTypes.object.isRequired,
  isUserLoaded: PropTypes.bool.isRequired,
};

export default UserAttributeList;
