import api from "~/shared/services/api";

export const listPosterComments = ({ page = 1, posterId = null }) =>
  Promise.resolve(
    api.get("/comment", { params: { page, poster_id: posterId } })
  );

export const createPosterComment = ({ commentData }) =>
  Promise.resolve(api.post("/comment", commentData));

export const deletePosterComment = ({ commentId }) =>
  Promise.resolve(api.delete(`/comment/${commentId}`));
