import React from "react";

import { Checkbox, Typography } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminGamification } from "../../hooks";

function SettingsTab() {
  const {
    handleEnableGamification,
    handleShowUserRanking,
    settings,
  } = useAdminGamification();
  const { isEnabled, showUserRanking } = settings;
  const { translate } = useTranslation();

  return (
    <div>
      <Typography className="opaque uppercase bold tiny">
        {translate("entities.gamification.settings")}
      </Typography>

      <div style={{ position: "relative", right: 8 }}>
        <Checkbox
          checked={isEnabled}
          onClick={handleEnableGamification}
          placeholder={translate("entities.gamification.enableGamification")}
        />

        {isEnabled && (
          <Checkbox
            checked={showUserRanking}
            onClick={handleShowUserRanking}
            placeholder={translate("entities.gamification.showUserRanking")}
          />
        )}
      </div>
    </div>
  );
}

export { SettingsTab };
