import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Infos } from "../../molecules";
import { SideMenu, sideMenuPropTypes } from "../SideMenu";
import { IconButton, Icon, Line, avatarPropTypes } from "../../atoms";
import {
  LanguagesMenu,
  languagesMenuPropTypes,
} from "../../molecules/LanguagesMenu";
import { SwipeableDrawer } from "./styles";

const drawerPropTypes = {
  show: PropTypes.bool.isRequired,
  /** Callback when show state change */
  onChange: PropTypes.func,
  /** Control all <b>Avatar</b> component props. To see all props see
   * <b>Avatar</b>.
   */
  avatar: PropTypes.shape(avatarPropTypes),
  /** Control all <b>LanguageMenu</b> component props. To see all props see
   * <b>Molecules/LanguageMenu>/b>.
   */
  languageMenu: PropTypes.shape(languagesMenuPropTypes),
  /** Callback when user click into <b>ReturnButton</b>. */
  onReturn: PropTypes.func,
  /** An array of arrays to be rendered at top of this component. */
  topNavigation: sideMenuPropTypes.topNavigation,

  className: PropTypes.string,

  style: PropTypes.object,

  userName: PropTypes.string,

  userSubTitle: PropTypes.string,
};

const drawerDefaultProps = {
  topNavigation: [],
  languageMenu: null,
  userName: null,
  userSubTitle: null,
  avatar: null,
  onChange: () => {},
  onReturn: () => {},
  className: "drawer",
  style: null,
};

/** This component implements <b>SideMenu</b> component and add an headerCoponent
 * and an animation. To see all props see <b>Organismis/SideMenu<b>
 */
function Drawer({
  show,
  style,
  avatar,
  onChange,
  className,
  onReturn,
  languageMenu,
  topNavigation,
  userName,
  userSubTitle,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);

  //* FUNCTIONS
  const toggleDrawer = openStatus => event => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    onChange(openStatus);
  };

  return (
    <SwipeableDrawer
      anchor="left"
      open={show}
      onClose={toggleDrawer(false)}
      onOpen={toggleDrawer(true)}
      className={className}
      style={style}
    >
      <SideMenu
        {...rest}
        topNavigation={topNavigation}
        headerComponent={
          <div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                position: "relative",
                bottom: "8px",
              }}
            >
              <IconButton
                onClick={onReturn}
                style={{ position: "relative", right: 16 }}
              >
                <Icon iconName="arrowBack" color="white" />
              </IconButton>

              {languageMenu && (
                <LanguagesMenu
                  {...languageMenu}
                  buttonSelectColor={colors.surface}
                  style={{ position: "relative", left: 8 }}
                />
              )}
            </div>

            <Infos
              avatar={{ ...avatar, size: 80 }}
              title={userName}
              subTitle={userSubTitle}
              titleStyles="white bold"
              subTitleStyles="white"
            />

            <Line style={{ marginTop: 16 }} />
          </div>
        }
        style={{ height: "100%", width: "100%" }}
      />
    </SwipeableDrawer>
  );
}

Drawer.propTypes = drawerPropTypes;

Drawer.propTypes = drawerPropTypes;

Drawer.defaultProps = drawerDefaultProps;

export { Drawer, drawerPropTypes, drawerDefaultProps };
