import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { SimpleMenu as SimpleMenuC } from "../../../molecules";
import { Card as CardC, Typography } from "../../../atoms";

export const Card = styled(CardC)`
  position: relative;
  display: block;
  padding: 8px;

  &:hover {
    .simple-menu,
    .like-button,
    .reply-button {
      opacity: 1;
      transition: opacity 0.3s ease;
    }

    .chat-like-button {
      opacity: 1;
    }
  }
`;
export const Container = styled.div``;

export const Message = styled(Typography)`
  word-break: break-word;

  ${props =>
    props.hasHighlight &&
    css`
      strong {
        background: ${props.theme.colors.accent};
        color: ${props.theme.colors.surface};
      }
    `}
`;

export const LikeButton = styled(CardC)`
  opacity: ${props => (props.isVisible ? 1 : 0)};
  cursor: pointer;
  position: absolute;
  bottom: -16px;
  left: 16px;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 8px 4px;
  height: 24px;
  border-radius: 4px;
  background: ${props => props.theme.colors.surface};
  color: ${props => props.theme.colors.accent};

  transition: all 0.3s;

  #_009-like {
    height: 14px;
    width: 14px;

    path {
      fill: ${props => props.theme.colors.accent};
    }
  }

  ${props =>
    props.isLiked &&
    css`
      background: ${props.theme.colors.accent};
      color: ${props.theme.colors.surface};

      #_009-like {
        path {
          fill: ${props.theme.colors.surface};
        }
      }
    `}
`;

export const SimpleMenu = styled(SimpleMenuC)`
  opacity: 0;
  transition: opacity 0.3s;

  position: absolute;
  top: -8px;
  right: -12px;
`;

export const ReplyButton = styled(Typography)`
  cursor: pointer;
  opacity: 0;

  position: absolute;
  bottom: -16px;
  right: 12px;
`;
