import React from "react";
import { useParams } from "react-router-dom";

import {
  openAdminDownloadMaterials,
  openFolderDetails,
  openCreateDownloadMaterials,
} from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useDownloadMaterial } from "../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";

import { Container, DownloadMaterialForm } from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function CreateMaterialForFolder() {
  const { folderId } = useParams();

  const { showFolder, folderTitle } = useDownloadMaterial();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderId)) {
      showFolder(folderId);
    }

    return () => {
      isMounted = false;
    };
  }, [folderId, showFolder]);

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.downloadMaterials.downloadableMaterials"),
        function: openAdminDownloadMaterials,
      },
      {
        id: 2,
        name: dTranslate(folderTitle),
        function: () => openFolderDetails(folderId),
      },
      {
        id: 3,
        name: translate("entities.downloadMaterials.addMaterial"),
        function: openCreateDownloadMaterials,
      },
    ]);
    setEditMode(true);

    return () => {
      setEditMode(false);
      setBreadCrumbs([]);
    };
  }, [
    dTranslate,
    folderId,
    folderTitle,
    setBreadCrumbs,
    setEditMode,
    translate,
  ]);

  return (
    <Container>
      <DownloadMaterialForm folderId={folderId} />
    </Container>
  );
}

export default CreateMaterialForFolder;
