import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useTranslation } from "../../../hooks/translation";
import {
  Typography,
  Box,
  TextArea,
  Icon,
  LinkButton,
  iconButtonPropTypes,
} from "../../atoms";
import { Card, HideButton, DeleteButton, HideContainer } from "./styles";

const pinnedMessage = {
  message: PropTypes.string.isRequired,

  /** If TRUE render an message with an icon indicates that the message is pinned,
   * default is "false"
   */
  isPinned: PropTypes.bool,

  /** If TRUE render the <b>HideCointainer</b> instead of the <b>PinnedMessage</b>,
   * default is "false"
   */
  isHide: PropTypes.bool,

  /** Render the message of the <b>HideCointainer</b> */
  hidePinnedMessage: PropTypes.string,

  /** Callback that is triggered when user click into <b>HideButton</b> */
  onHide: PropTypes.func,

  /** Callback that is triggered when user click into <b>HideButton</b> */
  onUnHide: PropTypes.func,

  /** If this props is implemented, render an <b>DeleteButton</b> at left of <b>HideButton</b>.
   *
   * Callback that is triggered when user click into <b>DeleteButton</b>
   *
   * By default this props is not implemented
   */
  deleteButton: PropTypes.shape(iconButtonPropTypes),
};

const pinnedMessageDefaultProps = {
  isPinned: false,
  isHide: false,
  hidePinnedMessage: null,
  onHide: () => {},
  onUnHide: () => {},
  deleteButton: null,
};

/** An Card with an pinned message, commonl used inside Chat */
function PinnedMessage({
  message,
  hidePinnedMessage,
  isPinned,
  isHide,
  onHide,
  deleteButton,
  onUnHide,
  ...rest
}) {
  const { translate } = useTranslation();

  //* STATES
  const [isHiding, setIsHiding] = React.useState(false);

  const handleHide = callback => {
    setIsHiding(true);

    setTimeout(() => {
      callback();
      setIsHiding(false);
    }, 300);
  };

  return !isHide ? (
    <Card {...rest} disableShadow isHiding={isHiding}>
      {isPinned && (
        <Box display="flex" alignItems="center" marginBottom="10px">
          <Icon iconName="pin" size={16} />

          <Typography className="small opaque" style={{ marginLeft: 4 }}>
            {translate("common.pinned")}
          </Typography>
        </Box>
      )}

      <TextArea value={message} />

      <HideButton
        onClick={() => handleHide(onHide)}
        title={translate("common.hide")}
      >
        <Icon iconName="arrowDown" size={16} />
      </HideButton>

      {deleteButton && (
        <DeleteButton {...deleteButton}>
          <Icon iconName="close" size={16} />
        </DeleteButton>
      )}
    </Card>
  ) : (
    <HideContainer
      isHiding={isHiding}
      display="flex"
      alignItems="center"
      {...rest}
    >
      <Icon iconName="pin" size={16} />

      <LinkButton
        variant="opaque"
        onClick={() => handleHide(onUnHide)}
        style={{ marginLeft: 4 }}
      >
        {hidePinnedMessage}
      </LinkButton>
    </HideContainer>
  );
}

PinnedMessage.propTypes = pinnedMessage;
PinnedMessage.defaultProps = pinnedMessageDefaultProps;

export { PinnedMessage, pinnedMessage, pinnedMessageDefaultProps };
