import React from "react";
import PropTypes from "prop-types";

//* VARIANTS
import Default from "./Default";
import ThreeDimensional from "./3d";

//* PROPS
import { propTypes } from "./propTypes";

const carrousalPropTypes = {
  /** An array of slide  */
  slides: propTypes.slides,
  variant: PropTypes.oneOf(["default", "3d"]),
};

const carrousalDefaultProps = {
  variant: "default",
};

/** A slideshow component for cycling through elements images or slides of text like a carousel. */
function Carrousal({ variant, slides, onClick, ...rest }) {
  const renderCarrousal = () => {
    switch (variant) {
      case "default":
        return <Default slides={slides} onClick={onClick} {...rest} />;

      case "3d":
        return <ThreeDimensional slides={slides} onClick={onClick} {...rest} />;

      default:
        return <Default slides={slides} onClick={onClick} {...rest} />;
    }
  };

  return renderCarrousal();
}

Carrousal.propTypes = carrousalPropTypes;
Carrousal.defaultProps = carrousalDefaultProps;

export { Carrousal, carrousalPropTypes, carrousalDefaultProps };
