import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { isListValid } from "../../../../utils/validations";

import {
  Container,
  Scroll,
  SpeakerAvatarTrim,
  SpanSpeakers,
  SpanSpeakerCell,
  SpeakerAvatar,
} from "./styles";

function SpeakerAvatarViewList({ className, speakers, maxSpeakers }) {
  return (
    isListValid(speakers) && (
      <Container className={className}>
        {speakers.slice(0, maxSpeakers).map(speaker => (
          <SpeakerAvatar key={speaker.id} src={speaker.picture} size={24} />
        ))}

        {speakers.length - 1 >= maxSpeakers && (
          <SpeakerAvatarTrim>
            ...
            <SpanSpeakers className="span-speakers">
              <Scroll>
                {isListValid(speakers) &&
                  speakers.map(
                    (speaker, index) =>
                      index >= maxSpeakers && (
                        <SpanSpeakerCell key={speaker.id} speaker={speaker} />
                      )
                  )}
              </Scroll>
            </SpanSpeakers>
          </SpeakerAvatarTrim>
        )}
      </Container>
    )
  );
}

SpeakerAvatarViewList.propTypes = {
  className: PropTypes.string,
  speakers: PropTypes.arrayOf(PropTypes.object).isRequired,
  maxSpeakers: PropTypes.number,
};

SpeakerAvatarViewList.defaultProps = {
  className: null,
  maxSpeakers: 3,
};

export default SpeakerAvatarViewList;
