import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";
import { ThemeContext } from "styled-components";

//* ACTIONS
import {
  createExtraButton,
  updateExtraButton,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

//* CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import history from "~/shared/services/history";
import Icons from "~/modules/signed/modules/cms/components/lists/IconList";
import { useTranslation } from "~/shared/hooks/translation";
import { Modal, Form, Input, TranslateInput, Footer } from "./styles";

const extraButtonModalPropTypes = {
  extraButton: PropTypes.object,
  refType: PropTypes.string.isRequired,
  refId: PropTypes.number.isRequired,
};

const extraButtonModalDefaultProps = {
  extraButton: null,
};

function ExtraButtonModal({ extraButton, refType, refId }) {
  const { translate } = useTranslation();

  //* REDUCER
  const dispatch = useDispatch();

  const { isCreating, isUpdating } = useSelector(
    state => state.adminExtraLinks.extraButtons
  );

  //* STATES
  const [show, setShow] = React.useState(true);
  const [formData, setFormData] = React.useState({});
  const [selectedIcon, setSelectedIcon] = React.useState("");

  const editMode = isDataValid(extraButton);
  const extraButtonId = editMode ? extraButton.id : null;

  //* FUNCTIONS
  const { colors } = React.useContext(ThemeContext);

  React.useEffect(() => {
    if (editMode) {
      setFormData(extraButton);
      setSelectedIcon(extraButton.type);
    }
  }, [editMode, extraButton]);

  const handleOnClose = () => {
    setShow(false);

    setTimeout(() => history.goBack(), 300);
  };

  const handleSubmit = data => {
    const extraButtonData = {
      ...data,
      ref_id: refId,
      ref_type: refType,
      order: 0,
      type: selectedIcon,
    };

    if (!editMode) {
      dispatch(createExtraButton(extraButtonData));
    } else {
      dispatch(updateExtraButton(extraButtonData, extraButtonId));
    }
  };

  return (
    <Modal
      show={show}
      onClose={handleOnClose}
      title={
        editMode
          ? `${translate("entities.marketRooms.buttons.edit")}`
          : `${translate("entities.marketRooms.buttons.add")}`
      }
      titleColor={colors.accent}
      withScroll
    >
      <Form
        onSubmit={data => handleSubmit(data)}
        yupValidation={{
          label: Yup.string().required(
            translate("entities.broadcastRooms.warnings.nameBroadcastButton")
          ),
          url: Yup.string().required(
            translate(
              "entities.broadcastRooms.warnings.linkBroadcastButtonLink"
            )
          ),
        }}
        initialData={formData}
      >
        <TranslateInput
          name="label"
          title={translate("entities.broadcastRooms.modalButton.name")}
          placeholder={translate(
            "entities.broadcastRooms.modalButton.placeholderName"
          )}
        />

        <Input
          name="url"
          title={translate("entities.broadcastRooms.modalButton.titleInput")}
          placeholder={translate(
            "entities.broadcastRooms.modalButton.placeholderInput"
          )}
        />

        <Icons setSelectedIcon={setSelectedIcon} selectedIcon={selectedIcon} />

        <Footer
          onCancel={handleOnClose}
          isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
        />
      </Form>
    </Modal>
  );
}

ExtraButtonModal.propTypes = extraButtonModalPropTypes;

ExtraButtonModal.defaultProps = extraButtonModalDefaultProps;

export {
  ExtraButtonModal,
  extraButtonModalPropTypes,
  extraButtonModalDefaultProps,
};
