import types from "./types";

// LIST
export function listMarketRooms() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listMarketRoomsSuccessful(marketRooms) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { marketRooms },
  };
}

// REORDER CATEGORY
export function reorderCategories({ reorderedList }) {
  return {
    type: types.CATEGORY.REORDER,
    payload: { reorderedList },
  };
}

// CREATE CATEGORY
export function craeteMarketRoomCategory(category) {
  return {
    type: types.CATEGORY.CREATE_SUCCESSFUL,
    payload: { category },
  };
}

// UPDATE CATEGORY
export function updateMarketRoomCategory(category, categoryId) {
  return {
    type: types.CATEGORY.UPDATE_SUCCESSFUL,
    payload: { category, categoryId },
  };
}

// DELETE CATEGORY
export function deleteMarketRoomCategory(categoryId) {
  return {
    type: types.CATEGORY.DELETE_SUCCESSFUL,
    payload: { categoryId },
  };
}
