import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { toast } from "react-toastify";

//* APIs
import {
  createLanguage as callCreateLanguage,
  updateLanguage as callUpdateLanguage,
} from "../../../../apis/languages";

//* CUSTOM IMPORTS;
import {
  ISO_COUNTRY_CODES,
  STATIC_LANGUAGES,
} from "~/shared/translation/constants";
import { useCMS } from "../../../../../../hooks";
import { Input, Typography, Box } from "~/shared/components/atoms";
import { Form, LanguagesMenu } from "~/shared/components/molecules";
import { Dropdown } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { popState } from "~/shared/services/history";

const languageFormPropTypes = {
  language: PropTypes.shape({
    id: PropTypes.number,
    title: PropTypes.string,
    flag_icon: PropTypes.string,
    static_language: PropTypes.string,
  }),
};

const languageFormDefaultProps = {
  language: null,
};

const LanguageForm = ({ language }) => {
  const { translate } = useTranslation();
  const { showFooterButtons, closeFooterButtons, setBreadCrumbs } = useCMS();
  const { addLanguage, updateLanguage } = useDynamicTranslation();

  //* STATES
  const [staticLanguage, setStaticLanguage] = React.useState(() => {
    if (language) {
      if (language.static_language) {
        const lang = Object.values(STATIC_LANGUAGES).find(
          staticLang => staticLang.code === language.static_language
        );

        return {
          code: language.static_language,
          name: lang ? lang.name : null,
        };
      }
    } else {
      return {
        code: null,
        name: null,
      };
    }
  });
  const [initialData, setInitialData] = React.useState({});
  const [isSubmiting, setIsSubmiting] = React.useState(false);
  const [flagIcon, setFlagIcon] = React.useState(ISO_COUNTRY_CODES.PORTUGUESE);

  //* FUNCTIONS
  React.useEffect(() => {
    if (language) {
      setInitialData(language);

      setFlagIcon(language.flag_icon);
    }
  }, [language]);

  React.useEffect(() => {
    showFooterButtons({
      formId: "languageForm",
      disableCancelButton: true,
      isLoading: isSubmiting,
    });

    setBreadCrumbs([
      { id: 1, name: translate("common.manageLanguages") },
      {
        id: 2,
        name: translate(
          language ? "common.editLanguage" : "common.addLanguage"
        ),
      },
    ]);

    return () => {
      closeFooterButtons();
      setBreadCrumbs([]);
    };
  }, [
    closeFooterButtons,
    isSubmiting,
    language,
    setBreadCrumbs,
    showFooterButtons,
    translate,
  ]);

  const handleOnSubmit = async data => {
    setIsSubmiting(true);

    const formmatedData = {
      ...data,
      flag_icon: flagIcon,
      static_language: staticLanguage.code,
    };

    if (!language) {
      await callCreateLanguage({ data: formmatedData })
        .then(response => {
          addLanguage(response.data);
          setIsSubmiting(false);
          toast.info(
            `${response.data.title} ${translate("common.successfullyAdded")}`
          );
          popState();
        })
        .catch(() => setIsSubmiting(false));
    } else {
      await callUpdateLanguage({
        data: formmatedData,
        languageId: language.id,
      })
        .then(response => {
          updateLanguage(response.data, response.data.id);
          setIsSubmiting(false);
          toast.info(
            `${response.data.title} ${translate("common.successfullyChanged")}`
          );
          popState();
        })
        .catch(() => setIsSubmiting(false));
    }
  };

  return (
    <Form
      initialData={initialData}
      onSubmit={handleOnSubmit}
      id="languageForm"
      yupValidation={{
        title: Yup.string().required(
          translate("components.input.languageName.isRequired")
        ),
        static_language: Yup.string().required(
          translate("components.select.languageStatic.isRequired")
        ),
      }}
      style={{ display: "flex" }}
    >
      <Box>
        <Typography
          className="uppercase normal bold small"
          style={{ marginBottom: 7 }}
        >
          {translate("common.icon")}
        </Typography>

        <LanguagesMenu
          listAllFlags
          buttonSize={32}
          buttonCountryCode={flagIcon}
          onFlagClick={flag => setFlagIcon(flag.countryCode)}
        />
      </Box>

      <Input
        title={translate("components.input.languageName.title")}
        name="title"
        placeholder={translate("components.input.languageName.placeholder")}
        style={{ marginLeft: 20, maxWidth: 450, zIndex: 2 }}
      />

      <Dropdown
        initialValue={staticLanguage.name}
        name="static_language"
        placeholder={translate("components.select.languageStatic.placeholder")}
        title={translate("components.select.languageStatic.title")}
        options={Object.values(STATIC_LANGUAGES).map(staticLang => ({
          value: staticLang.code,
          name: staticLang.name,
        }))}
        onChange={(_, option) =>
          setStaticLanguage({ code: option.value, name: option.name })
        }
        style={{ marginLeft: 16 }}
      />
    </Form>
  );
};

LanguageForm.propTypes = languageFormPropTypes;
LanguageForm.defaultProps = languageFormDefaultProps;

export { LanguageForm };
