import styled from "styled-components";

// CUSTOM IMPORTS
import { LoaderContainer } from "~/shared/components/molecules";

export const Container = styled.div``;

export const ContentLoader = styled(LoaderContainer)`
  #loader-list {
    flex-direction: column;
  }

  .loader-cell {
    margin-bottom: 16px;
  }
`;

export const Fieldset = styled.div`
  margin-bottom: 20px;

  h4 {
    font-size: 10px;
    font-weight: bold;
    color: ${props => props.theme.colors.opaque};
    margin-bottom: 8px;
    text-transform: uppercase;
  }

  p {
    margin: 0;
    font-size: 16px;
    color: ${props => props.theme.colors.strong};

    small {
      font-size: 16px;
      color: ${props => props.theme.colors.opaque};
      font-style: italic;
    }
  }
`;
