import React from "react";
import * as Yup from "yup";
import { Form } from "@unform/web";
import { FaWhatsapp } from "react-icons/fa";

//* APIs
import {
  createVariable,
  getVariable,
} from "~/modules/signed/modules/cms/modules/admin/apis/variables";

//* CUSTOM IMPORTS
import { useApp } from "~/shared/hooks";
import { whatsAppButton } from "~/shared/utils/variables";
import { getYupErrors } from "~/shared/utils/forms";
import { isDataValid } from "~/shared/utils/validations";
import { Input, Button, Spinner, Trans } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

function SupportTab() {
  const formRef = React.useRef(null);

  //* STATES
  const { setWhatsAppButton } = useApp();
  const [initialData, setInitialData] = React.useState({
    phone: null,
    message: null,
  });
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isSaving, setIsSaving] = React.useState(false);

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const fetch = async () => {
      await getVariable({ key: whatsAppButton.KEY }).then(response => {
        if (isDataValid(response.data.value)) {
          setInitialData({
            link: response.data.value,
          });
        }
      });

      setIsLoaded(true);
    };

    fetch();
  }, []);

  const handleSubmit = React.useCallback(
    async data => {
      const saveLink = async () => {
        setIsSaving(true);

        await createVariable({
          key: whatsAppButton.KEY,
          value: data.link,
        }).then(() => {
          setWhatsAppButton(data.link);
        });

        setIsSaving(false);
      };

      try {
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const schema = Yup.object().shape({
          link: Yup.string()
            .trim()
            .url(translate("handlers.link.isUrlInvalid")),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        saveLink();
      } catch (err) {
        const errors = getYupErrors(err);

        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [setWhatsAppButton, translate]
  );

  if (!isLoaded) return <Spinner centered />;

  return (
    <Form
      ref={formRef}
      style={{ maxWidth: 400 }}
      onSubmit={handleSubmit}
      initialData={initialData}
    >
      <Input
        title={translate("components.input.whatsappLink.title")}
        name="link"
        placeholder={translate("components.input.whatsappLink.placeholder")}
        style={{ marginTop: 16, marginBottom: 16 }}
        icon={<FaWhatsapp />}
        isRequired
      />

      <Button
        loading={isSaving}
        style={{ marginTop: 20, width: "40%", marginLeft: "auto" }}
        type="submit"
      >
        <Trans translationKey="common.save">Salvar</Trans>
      </Button>
    </Form>
  );
}

export { SupportTab };
