import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Scroll, List, Cell } from "./styles";
import { isListValid } from "../../../utils/validations";

const cellListPropTypes = {
  className: PropTypes.string,

  /** Array of items to be displayed */
  data: PropTypes.array.isRequired,

  /** An callback to render the list cells, return the value of data[index] */
  renderCell: PropTypes.func.isRequired,

  /** An callback to render loaders */
  renderCellLoader: PropTypes.func,

  /** List orientation can be "column": VERTICAL or "row": HORIZONTAL, "row" by default */
  orientation: PropTypes.oneOf(["column", "row"]),

  /** An boolean that controls if the list will call the renderCells function
   * when TRUE
   * or the renderCellLoader function when FALSE */
  isLoaded: PropTypes.bool,

  /** An boolean that display loaders on the footer when TRUE */
  isPaging: PropTypes.bool,

  /** Control how many loaders will be display when list is loading, only works
   * if "renderCellLoader" is implemented, 3 by default */
  loaderListSize: PropTypes.number,

  /** Add style to all cells in list */
  cellStyle: PropTypes.object,

  /** Add an gap in 'px' between cells, default is 0px */
  gap: PropTypes.number,

  /** Add style into list */
  listStyle: PropTypes.object,

  /** Render an component when list is empty */
  emptyComponent: PropTypes.element,

  /** Render an component at the top of the list */
  headerComponent: PropTypes.element,

  /** Render an component as the first cell in the list */
  firstCell: PropTypes.element,

  /** Render an component as the last cell in the list */
  lastCell: PropTypes.element,

  /** Render an component at the bottom of the list */
  footerComponent: PropTypes.element,

  /** Id of the cell that has to be manipulated */
  cellId: PropTypes.number,

  /** If TRUE replace cell corresponding to "cellId" with an cellLoader */
  isDeleting: PropTypes.bool,

  /** If TRUE replace cell corresponding to "cellId" with an cellLoader */
  isUpdating: PropTypes.bool,

  /** If TRUE render an cellLoader at the top of the list */
  isCreating: PropTypes.bool,

  /** If is TRUE add an inner scroll into list, FALSE by default */
  withScroll: PropTypes.bool,

  /** Add styles into scroll, only works with "withScroll" as TRUE */
  scrollStyle: PropTypes.object,

  /** Callback when scroll reaches the end only works with "withScroll" as TRUE */
  onBottomReached: PropTypes.func,

  /** Controls the styles of the <b>Container</b> thats disableWrap the list */
  containerStyle: PropTypes.object,

  /** If this props is <b>true</b> disable wrap the list */
  disableWrap: PropTypes.bool,

  /** Callback that return the scroll ref */
  containerRef: PropTypes.func,

  /** If <b>true</b> any scroll listener will be disabled,
   * <b>onBottomReached</b> will not work
   */
  disableScrollListener: PropTypes.bool,
};

const cellListDefaultProps = {
  className: null,
  renderCellLoader: () => {},
  orientation: "row",
  isLoaded: false,
  isPaging: false,
  loaderListSize: 3,
  cellStyle: null,
  gap: 0,
  listStyle: null,
  emptyComponent: null,
  headerComponent: null,
  firstCell: null,
  lastCell: null,
  footerComponent: null,
  cellId: null,
  isDeleting: false,
  isUpdating: false,
  isCreating: false,
  withScroll: false,
  scrollStyle: null,
  onBottomReached: () => {},
  containerStyle: null,
  disableWrap: false,
  containerRef: () => {},
  disableScrollListener: false,
};

/** A List Template to render any type of cell
 *
 * You can use this component to render simple or complex cells
 *
 */
function CellList({
  className,
  data,
  renderCell,
  renderCellLoader,
  orientation,
  isLoaded,
  isPaging,
  loaderListSize,
  gap,
  disableWrap,
  cellStyle,
  listStyle,
  scrollStyle,
  emptyComponent,
  headerComponent,
  firstCell,
  lastCell,
  footerComponent,
  withScroll,
  cellId,
  isUpdating,
  isDeleting,
  isCreating,
  onBottomReached,
  containerStyle,
  containerRef,
  disableScrollListener,
}) {
  function setLoaders() {
    const loaderArray = [];

    for (let i = 0; i < loaderListSize; i += 1) {
      loaderArray.push(i);
    }

    return loaderArray;
  }

  // FUNCTIONS
  const loaders = setLoaders();

  const handleRenderCell = (item, index) => {
    // If is deleting or updating change current cell to a loader
    if (isUpdating || isDeleting)
      if (item.id === cellId)
        return (
          <Cell key={`${item.id}-${index}`} style={cellStyle}>
            {renderCellLoader(item, index)}
          </Cell>
        );

    return (
      <Cell
        className="cell-list-cell"
        key={`${item.id}-${index}`}
        style={cellStyle}
      >
        {renderCell(item, index)}
      </Cell>
    );
  };

  function renderList() {
    return (
      <Container
        id="base-list"
        className={!withScroll && className}
        orientation={orientation}
        style={containerStyle}
      >
        {headerComponent}

        {isLoaded ? (
          <List
            disableWrap={disableWrap}
            withScroll={withScroll}
            gap={gap}
            style={{ flexDirection: orientation, ...listStyle }}
          >
            {firstCell}

            {isCreating && (
              <Cell className="cell-list-cell" style={cellStyle}>
                {renderCellLoader()}
              </Cell>
            )}

            {isListValid(data)
              ? data.map(
                  (item, index) =>
                    item !== null && handleRenderCell(item, index)
                )
              : !isCreating && emptyComponent}

            {isPaging &&
              loaders.map((loader, index) => (
                <Cell className="cell-list-cell" key={loader} style={cellStyle}>
                  {renderCellLoader(loader, index)}
                </Cell>
              ))}

            {lastCell}
          </List>
        ) : (
          <List
            disableWrap={disableWrap}
            withScroll={withScroll}
            gap={gap}
            style={{ flexDirection: orientation, ...listStyle }}
          >
            {loaders.map((loader, index) => (
              <Cell key={loader} style={cellStyle}>
                {renderCellLoader(loader, index)}
              </Cell>
            ))}
          </List>
        )}

        {footerComponent}
      </Container>
    );
  }

  return withScroll ? (
    <Scroll
      containerRef={containerRef}
      orientation={orientation}
      onYReachEnd={!disableScrollListener && !isPaging && onBottomReached}
      className={className}
      id="base-list-scroll"
      style={scrollStyle}
    >
      {renderList()}
    </Scroll>
  ) : (
    renderList()
  );
}

CellList.propTypes = cellListPropTypes;

CellList.defaultProps = cellListDefaultProps;

export { CellList, cellListPropTypes, cellListDefaultProps };
