import React from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  showUser,
  clearUser,
} from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import UserForm from "~/modules/signed/modules/cms/modules/admin/pages/Users/pages/components/UserForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminUsers, openUserDetails } from "~/shared/routes/navigation";
import { Spinner } from "~/shared/components/atoms";

function EditUser() {
  const dispatch = useDispatch();
  const { data: user, isLoaded } = useSelector(
    state => state.adminUsers.object
  );

  const { userId } = useParams();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    dispatch(showUser(userId));

    return () => {
      dispatch(clearUser());
    };
  }, [dispatch, userId]);

  React.useEffect(() => {
    if (isLoaded) {
      setBreadCrumbs([
        {
          id: 1,
          name: "Usuários",
          function: openAdminUsers,
        },
        {
          id: 2,
          name: isDataValid(user) && user.name,
          function: () => openUserDetails(userId, user.name),
        },
        {
          id: 2,
          name: "Editar Usuário",
          function: () => {},
        },
      ]);

      setEditMode(true);
    }

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [isLoaded, setBreadCrumbs, setEditMode, user, userId]);

  return isLoaded ? <UserForm user={user} /> : <Spinner centered />;
}

export default EditUser;
