import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { ChatMessageCell, chatCellPropTypes } from "../cells";
import { CellList, chatInputPropTypes } from "../../molecules";
import { Icon } from "../../atoms";
import Loader from "../../molecules/cellLoaders/CardLoader";
import { Container, ChatInput, Line } from "./styles";

const commentListPropTypes = {
  /** An array of chatCell object. See the structure in <b>Organisms/cells/ChatMessageCell</b>. */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      /** If <b>true</b> hide the <b>SimpleMenu</b> for this cell. */
      disableMenu: PropTypes.bool,
      /** If <b>true</b> change the color of <b>LikeButton</b>. */
      isLiked: PropTypes.bool,
      ...chatCellPropTypes,
    })
  ).isRequired,
  className: PropTypes.string,
  /** Callback when user send an comment. return the value of comment. */
  onComment: PropTypes.func,
  /** Callback when user scrolls to bottom of the list. */
  onBottomReached: PropTypes.func,
  /** Callback when user likes an comment. Return the comment data. */
  onLike: PropTypes.func,
  /** Callback when user clicks to reply an comment. Return the comment data. */
  onReply: PropTypes.func,
  /** Callback when user clicks to delete an comment. Return the comment data. */
  onDelete: PropTypes.func,
  style: PropTypes.object,
  /** Control all props for <b>ChatInput</b>.See all props in <b>Molecules/ChatCell</b>. */
  chatInput: PropTypes.shape(chatInputPropTypes),
  /** Control the maxHeight for scroll be displayed. */
  scrollHeight: PropTypes.number,
};

const commentListDefaultProps = {
  className: "comment-list",
  style: null,
  chatInput: null,
  scrollHeight: null,
  onComment: () => {},
  onBottomReached: () => {},
  onLike: () => {},
  onReply: () => {},
  onDelete: () => {},
};

/** An list of comment, with an <b>ChatInput</b>. This component use <b>CellList</b>
 * to see all props see <b>Molecules/CellList</b>.
 */
const CommentList = ({
  data,
  onComment,
  onBottomReached,
  onLike,
  onReply,
  className,
  style,
  chatInput,
  scrollHeight,
  onDelete,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);

  //* FUNCTIONS
  const renderCell = (comment, index) => {
    return (
      <ChatMessageCell
        {...comment}
        disableReply
        isLiked={comment.isLiked}
        simpleMenu={
          !comment.disableMenu && {
            fillItems: () => [
              {
                id: 1,
                name: "Excluir comentário",
                function: () => onDelete(comment),
                color: colors.redWrong,
                icon: <Icon iconName="delete" />,
              },
            ],
          }
        }
        onLikeButtonClick={() => onLike(comment)}
        onReplyClick={() => onReply(comment)}
        style={{ marginBottom: index === data.length - 1 && 16 }}
      />
    );
  };

  return (
    <Container className={className} stye={style}>
      <CellList
        {...rest}
        data={data}
        withScroll
        onBottomReached={onBottomReached}
        noScrollReposition
        listStyle={{ maxHeight: scrollHeight, padding: "0 16px 8px" }}
        orientation="column"
        renderCell={renderCell}
        renderCellLoader={() => (
          <Loader height={45} style={{ width: "100%" }} />
        )}
        gap={24}
      />

      <Line />

      <ChatInput
        {...chatInput}
        className="comment-list-input"
        onSubmit={onComment}
      />
    </Container>
  );
};

CommentList.propTypes = commentListPropTypes;

CommentList.defaultProps = commentListDefaultProps;

export { CommentList, commentListPropTypes, commentListDefaultProps };
