/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listGroupsSuccessful,
  createGroupSuccessful,
  createGroupFailed,
  updateGroupFailed,
  updateGroupSuccessful,
  deleteGroupFailed,
  deleteGroupSuccessful,
  clearGroupForm,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/admin/group");

    if (validResponseStatus(response.status)) {
      const groups = response.data;

      yield put(listGroupsSuccessful(groups));
    }
  } catch (err) {
    console.log(err);
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { group } = payload;

    const response = yield call(api.post, "/admin/group", group);

    if (validResponseStatus(response.status)) {
      const newGroup = response.data;

      yield put(createGroupSuccessful(newGroup));
    }

    yield put(clearGroupForm());
  } catch (err) {
    yield put(createGroupFailed());
    console.log(err);
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { groupId, group } = payload;

    const response = yield call(api.put, `/admin/group/${groupId}`, group);

    if (validResponseStatus(response.status)) {
      const updatedGroup = response.data;

      yield put(updateGroupSuccessful(updatedGroup));
    }

    yield put(clearGroupForm());
  } catch (err) {
    yield put(updateGroupFailed());
    console.log(err);
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { groupId } = payload;

    const response = yield call(api.delete, `/admin/group/${groupId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteGroupSuccessful(groupId));
    }

    yield put(clearGroupForm());
  } catch (err) {
    yield put(deleteGroupFailed());
    console.log(err);
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
]);
