import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  DynamicField,
  dynamicFieldPropTypes,
} from "~/modules/signed/modules/client/components/DynamicField";
import { Icon } from "~/shared/components/atoms";
import {
  Container,
  Fieldset,
  Form,
  Title,
  Buttons,
  IconButton,
} from "./styles";

const registrationFieldCellPropTypes = {
  /** Callback that is triggered when user clicks into <b>DeleteButton</b> */
  onDelete: PropTypes.func,

  /** Callback that is triggered when user clicks into <b>EditButton</b> */
  onEdit: PropTypes.func,

  /** Implements all props for <b>DynamicField</b> components */
  dynamicField: PropTypes.shape(dynamicFieldPropTypes).isRequired,

  /** Add an <b>*</b> at right of title */
  isRequired: PropTypes.bool,

  title: PropTypes.string.isRequired,

  /** Control the <b>zIndex</b> for this component */
  index: PropTypes.number,
};

const registrationFieldCellDefaultProps = {
  onDelete: null,
  onEdit: null,
  isRequired: false,
  index: 0,
};

/** This component is an <b>DynamicField<b> with an drag, delete and edit buttons */
const RegistrationFieldCell = ({
  index,
  title,
  isRequired,
  onDelete,
  onEdit,
  dynamicField,
  ...rest
}) => (
  <Container {...rest} index={index}>
    <Title>
      {title}
      {isRequired && <span>*</span>}
    </Title>

    <Fieldset>
      <Icon iconName="drag" />

      <Form>
        <DynamicField {...dynamicField} />
      </Form>

      <Buttons>
        {onEdit && (
          <IconButton onClick={onEdit}>
            <Icon iconName="edit" />
          </IconButton>
        )}

        {onDelete && (
          <IconButton onClick={onDelete}>
            <Icon iconName="close" />
          </IconButton>
        )}
      </Buttons>
    </Fieldset>
  </Container>
);

RegistrationFieldCell.propTypes = registrationFieldCellPropTypes;

RegistrationFieldCell.defaultProps = registrationFieldCellDefaultProps;

export {
  RegistrationFieldCell,
  registrationFieldCellPropTypes,
  registrationFieldCellDefaultProps,
};
