import React from "react";
import PropTypes from "prop-types";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { medias } from "../../../../styles";

const CommentCellLoader = ({ windowWidth }) => {
  const { colors } = React.useContext(ThemeContext);

  function randomInt(min, max) {
    return min + Math.floor((max - min) * Math.random());
  }

  const isMobile = windowWidth <= medias.MOBILE;
  const maxWidth = windowWidth <= medias.MOBILE ? windowWidth : 400;

  const width = randomInt(100, isMobile ? windowWidth - 100 : 300);

  return (
    <ContentLoader
      speed={2}
      width={maxWidth}
      height={70}
      viewBox={`0 0 ${maxWidth} 70`}
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
    >
      <rect x="0" y="0" rx="8" ry="8" width="40" height="40" />
      <rect x="50" y="0" rx="8" ry="8" width={width} height="70" />
    </ContentLoader>
  );
};

CommentCellLoader.propTypes = {
  windowWidth: PropTypes.number.isRequired,
};

export default CommentCellLoader;
