import React from "react";

//* CUSTOM IMPORTS
import { useAdminEmail } from "./hooks";
import { EMAIL_TYPES } from "~/shared/utils/types";
import { Container, EmailList as EmailListC } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

import {
  openDetailEmail,
  openEditEmail,
  openCreateEmail,
} from "~/shared/routes/navigation";

const EmailList = ({ ...rest }) => {
  const {
    deleteAdminEmail,
    createAdminEmail,
    paginateEmails,
  } = useAdminEmail();

  const handleOnClone = data => {
    createAdminEmail({
      ...data,
      type: EMAIL_TYPES.SKETCH,
      send_now: false,
      schedule_date: null,
    });
  };

  const handleOnDelete = (data, type) => deleteAdminEmail(data.id, type);

  const handleOnPaginate = type => paginateEmails(type);

  const handleOnEdit = email => openEditEmail({ emailId: email.id });

  const handleOnAdd = () => openCreateEmail();

  const handleOnClick = email => {
    if (email.type === EMAIL_TYPES.SENDED) {
      openDetailEmail({ emailId: email.id });
      return;
    }

    handleOnEdit(email);
  };

  return (
    <EmailListC
      {...rest}
      onAdd={handleOnAdd}
      onEdit={handleOnEdit}
      onDelete={handleOnDelete}
      onClick={handleOnClick}
      onPaginate={handleOnPaginate}
      onClone={handleOnClone}
    />
  );
};

const Emails = () => {
  //* FUNCTIONS
  const { translate } = useTranslation();

  const { sendedEmails, scheduleEmails, sketchEmails } = useAdminEmail();

  return (
    <Container>
      <EmailList
        title={translate("entities.emails.new")}
        emails={sketchEmails.data}
        isLoaded={sketchEmails.isLoaded}
        emailType={EMAIL_TYPES.SKETCH}
        isDeleting={sketchEmails.isDeleting}
        isPaging={sketchEmails.isPaging}
        isCreating={sketchEmails.isCreating}
        disablePagination={sketchEmails.page >= sketchEmails.lastPage}
      />

      <EmailList
        title={translate("entities.emails.scheduledPlural")}
        emails={scheduleEmails.data}
        isLoaded={scheduleEmails.isLoaded}
        emailType={EMAIL_TYPES.SCHEDULE}
        isDeleting={scheduleEmails.isDeleting}
        isPaging={scheduleEmails.isPaging}
        isCreating={scheduleEmails.isCreating}
        disablePagination={scheduleEmails.page >= scheduleEmails.lastPage}
        disableAddButton
        empty={{
          message: translate("entities.emails.scheduled.empty.message"),
          description: translate("entities.emails.scheduled.empty.description"),
        }}
      />

      <EmailList
        title={translate("entities.emails.sendedPlural")}
        emails={sendedEmails.data}
        isLoaded={sendedEmails.isLoaded}
        emailType={EMAIL_TYPES.SENDED}
        isDeleting={sendedEmails.isDeleting}
        isPaging={sendedEmails.isPaging}
        isCreating={sendedEmails.isCreating}
        disablePagination={sendedEmails.page >= sendedEmails.lastPage}
        disableAddButton
        disableEditButton
        empty={{
          message: translate("entities.emails.sended.empty.message"),
          description: translate("entities.emails.sended.empty.description"),
        }}
      />
    </Container>
  );
};

export default Emails;
