import React from "react";
import PropTypes from "prop-types";
import { MdSend, MdLock, MdTagFaces, MdClose } from "react-icons/md";

//* CUSTOM IMPORTS
import { IconButton } from "../../atoms";
import { replyMessageCardPropTypes } from "..";
import { isStringEmpty } from "../../../utils/strings";
import { ProgressBar } from "../../atoms/ProgressBar";
import { useTranslation } from "../../../hooks/translation";
import {
  Container,
  InputContainer,
  InputMessage,
  SubmitButton,
  ReplyMessageCard,
  Limiter,
  SpamContainer,
  SpamLabel,
  EmojiPicker,
  ReplyMessageScroll,
} from "./styles";
import { propTypes } from "../Carrousal/propTypes";
import { useWindow } from "../../../hooks/window";

const chatInputPropTypes = {
  placeholder: PropTypes.string.isRequired,
  /** Callback that is triggered when user submit inside de <b>Chat Input</b>,
   *  return the input value */
  onSubmit: PropTypes.func,
  /** If TRUE, block user to type into the input and add an RED border in
   *  <b>Chat Input</b>, default is "false" */
  isBlocked: PropTypes.bool,
  /** Implement <b>ReplyMessageCard</b> component see more in molecules/ReplyMessageCard */
  replyMessageCard: PropTypes.shape(replyMessageCardPropTypes),
  /** Define the max length for the input. */
  limit: PropTypes.number,
  /** Number of messages to active checkSpam, default is 3 */
  spamValue: propTypes.number,
  /** Time interval to archive spamValue (in miliseconds) */
  spamTimeout: propTypes.number,
  /** Time to block user (in miliseconds) */
  spamBlock: propTypes.number,
  /** An id to track the input */
  inputId: PropTypes.string,
  /** Enable shift + enter jump line, default is <b>false</b> */
  withPreLine: PropTypes.bool,
};

const chatInputDefaultProps = {
  onSubmit: () => {},
  isBlocked: false,
  replyMessageCard: null,
  limit: null,
  spamValue: null,
  spamTimeout: 4000,
  spamBlock: 5000,
  inputId: null,
  withPreLine: false,
};

/** Chat Input is commonly used in Chat, has limit char validation */
function ChatInput({
  inputId,
  onSubmit,
  isBlocked,
  replyMessageCard,
  placeholder,
  limit,
  spamValue,
  spamTimeout,
  spamBlock,
  withPreLine,
  ...rest
}) {
  const { isMobile } = useWindow();

  //* STATES
  const [value, setValue] = React.useState("");
  const [showEmojisPicker, setShowEmojisPicker] = React.useState();
  const [messagesNumber, setMessagesNumber] = React.useState(0);
  const [seconds, setSeconds] = React.useState();
  const [isFocused, setIsFocused] = React.useState(false);

  const isSpam = spamValue && messagesNumber >= spamValue;
  const isLimitReached = limit && value.length >= limit;

  //* FUNCTIONS
  const { translate } = useTranslation();

  // reset messagesNumber in spamTimeout mileseconds
  React.useEffect(() => {
    const interval = setInterval(
      () => !isSpam && setMessagesNumber(0),
      spamTimeout
    );
    return () => clearInterval(interval);
  }, [isSpam, spamTimeout]);

  React.useMemo(() => {
    if (isSpam) setTimeout(() => setMessagesNumber(0), spamBlock);
  }, [isSpam, spamBlock]);

  React.useEffect(() => {
    const interval = setInterval(() => {
      if (isSpam) {
        if (seconds > 0) setSeconds(seconds - 1000);
      } else {
        setSeconds(spamBlock);
      }
    }, 1000);
    return () => clearInterval(interval);
  }, [isSpam, seconds, spamBlock]);

  const handleSubmit = () => {
    setMessagesNumber(messagesNumber + 1);
    if (!isStringEmpty(value) && !isBlocked && !isLimitReached && !isSpam) {
      setValue("");

      onSubmit(value);
      setShowEmojisPicker(false);
    }
  };

  const handleInputMessageKeyDown = e => {
    if (e.keyCode === 13) {
      if (withPreLine) {
        if (!e.shiftKey && !isMobile) {
          e.preventDefault();

          handleSubmit();
        }
      } else {
        e.preventDefault();

        handleSubmit();
      }
    }
  };

  const renderLimit = () => {
    if (isLimitReached) return "reached";

    if (value.length >= limit - limit * 0.2) return "reaching";

    return "normal";
  };

  const handleEmojiPickerOpened = () =>
    showEmojisPicker ? setShowEmojisPicker(false) : setShowEmojisPicker(true);

  const onSelect = emoji => {
    setValue(value + emoji.native);
  };

  return (
    <>
      <SpamContainer isSpam={isSpam}>
        {isSpam && (
          <SpamLabel>
            {translate("components.chatInput.spamMessage")}
            {seconds / 1000}
            {translate("components.chatInput.seconds")}
          </SpamLabel>
        )}

        <Container
          className="chat-input"
          isBlocked={isBlocked || isSpam}
          isFocused={isFocused}
          {...rest}
        >
          {showEmojisPicker && !isSpam && !isBlocked && (
            <EmojiPicker
              style={{ width: "100%" }}
              className="chat-input-emojis"
              onClick={emoji => onSelect(emoji)}
            />
          )}

          {replyMessageCard && replyMessageCard.message && (
            <ReplyMessageScroll>
              <ReplyMessageCard {...replyMessageCard} />
            </ReplyMessageScroll>
          )}

          {isSpam && <ProgressBar animationTime={spamBlock / 1000} />}

          <InputContainer>
            <IconButton
              className="emoji-picker"
              disabled={isBlocked || isSpam}
              onClick={handleEmojiPickerOpened}
            >
              {showEmojisPicker ? <MdClose /> : <MdTagFaces />}
            </IconButton>

            <InputMessage
              id={`chat-input-${inputId}`}
              className="chat-text-area"
              enabled={!isBlocked && !isSpam}
              onKeyDown={e => handleInputMessageKeyDown(e)}
              type="text"
              isBlocked={!!isBlocked}
              value={value}
              placeholder={placeholder}
              onChange={e => setValue(e.target.value)}
              onFocus={() => setIsFocused(true)}
              onBlur={() => setIsFocused(false)}
            />

            {limit && (
              <Limiter
                variant={renderLimit()}
              >{`${value.length}/${limit}`}</Limiter>
            )}

            <SubmitButton
              className="send-message-button"
              onClick={handleSubmit}
              isDisabled={isStringEmpty(value) || isLimitReached}
              disabled={isBlocked || isLimitReached || isSpam}
              type="button"
              title={translate("components.chatInput.submit")}
            >
              {isBlocked ? <MdLock /> : <MdSend />}
            </SubmitButton>
          </InputContainer>
        </Container>
      </SpamContainer>
    </>
  );
}

ChatInput.propTypes = chatInputPropTypes;

ChatInput.defaultProps = chatInputDefaultProps;

export { ChatInput, chatInputPropTypes, chatInputDefaultProps };
