import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  BreadCrumbs,
  footerButtonsPropTypes,
  breadCrumbsPropTypes,
} from "../../molecules";
import { Wrapper, ScrollCard, Title, Children, FooterButtons } from "./styles";

const cmsTemplatePropTypes = {
  children: PropTypes.element.isRequired,

  scroll: {
    onBottomReached: PropTypes.func,
    editMode: PropTypes.bool,
    ref: PropTypes.func,
  },

  title: PropTypes.string,

  breadCrumbs: PropTypes.shape(breadCrumbsPropTypes),

  footerButtons: PropTypes.shape(footerButtonsPropTypes),

  sideMenuComponent: PropTypes.element,
};

const cmsTemplateDefaultProps = {
  scroll: {
    onBottomReached: () => {},
    editMode: false,
    ref: () => {},
  },
  title: null,
  breadCrumbs: [],
  footerButtons: null,
  sideMenuComponent: null,
};

const CMSTemplate = ({
  children,
  scroll,
  sideMenuComponent,
  title,
  breadCrumbs,
  footerButtons,
}) => {
  return (
    <Wrapper>
      {sideMenuComponent}

      <ScrollCard
        onYReachEnd={scroll.onBottomReached}
        containerRef={ref => {
          scroll.ref(ref);
        }}
        id="admin-card"
        editMode={!!footerButtons || scroll.editMode}
      >
        {title && <Title id="admin-card-title">{title}</Title>}

        <BreadCrumbs {...breadCrumbs} style={{ margin: " 10px 0 32px 0" }} />

        <Children>
          {children}

          <FooterButtons {...footerButtons} />
        </Children>
      </ScrollCard>
    </Wrapper>
  );
};

CMSTemplate.propTypes = cmsTemplatePropTypes;

CMSTemplate.defaultProps = cmsTemplateDefaultProps;

export { CMSTemplate, cmsTemplatePropTypes, cmsTemplateDefaultProps };
