import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { FaPlus, FaMinus, MdClose } from "react-icons/all";

//* APIs
import { getCertificatePreview } from "~/modules/signed/modules/cms/modules/admin/apis/certificates";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { Spinner } from "~/shared/components/atoms";
import { toHtml } from "~/shared/utils/strings";
import {
  ZoomInButton,
  ZoomOutButton,
  Buttons,
  CloseButton,
  Container,
} from "./styles";

const useStyles = makeStyles(() => ({
  modal: {
    padding: 20,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
}));

const certificateDimensions = { width: 1366, height: 965 };
const backdropPadding = 80;

export default function CertificatePreviewModal({
  show,
  onClose,
  bodyMessage,
  background,
  footer,
  showSchedule,
  broadcastRoomIds,
}) {
  const classes = useStyles();

  //* STATES
  const [zoom, setZoom] = React.useState(1);
  const { status, data: certificateHtml, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  React.useEffect(() => {
    run(
      getCertificatePreview({
        bodyMessage,
        background,
        footer,
        showSchedule,
        broadcastRoomIds,
      })
    );
  }, [background, bodyMessage, broadcastRoomIds, footer, run, showSchedule]);

  //* Calculate the initial zoom level based on user window width*/
  React.useEffect(() => {
    const windowHeigth = document.body.clientHeight;
    const windowWidth = document.body.clientWidth;
    const { width } = certificateDimensions;
    const { height } = certificateDimensions;

    //* Resize certificate preview based on heigth
    if (windowHeigth < height + backdropPadding) {
      const difference = height + backdropPadding - windowHeigth;
      const scaleSubtraction = difference / 1000;

      setZoom(1 - scaleSubtraction);
    }

    //* Resize certificate preview based on width
    if (windowWidth < width) {
      const difference = width - windowWidth;
      const scaleSubtraction = difference / 1000;

      setZoom(1 - scaleSubtraction);
    }
  }, []);

  const handleClose = () => {
    onClose();
  };

  const onZoomIn = () => {
    setZoom(zoom + 0.1);
  };

  const onZoomOut = () => {
    setZoom(zoom - 0.1);
  };

  return (
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={classes.modal}
      open={show}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
      style={{ padding: 0 }}
    >
      <Fade in={show}>
        <div
          className=".ql-editor"
          style={{ overflowY: "scroll", maxHeight: "100vh", width: "100%" }}
        >
          {isLoaded ? (
            <>
              <CloseButton
                type="button"
                title="Fechar Visualização"
                onClick={onClose}
              >
                <MdClose />
              </CloseButton>

              <Buttons>
                <ZoomInButton
                  className="zoom-button"
                  type="button"
                  title="Aumentar zoom"
                  onClick={onZoomIn}
                >
                  <FaPlus />
                </ZoomInButton>

                <ZoomOutButton
                  className="zoom-button"
                  type="button"
                  title="Diminuir zoom"
                  onClick={onZoomOut}
                >
                  <FaMinus />
                </ZoomOutButton>
              </Buttons>

              <Container zoom={zoom}>
                <div
                  dangerouslySetInnerHTML={toHtml({ string: certificateHtml })}
                />
              </Container>
            </>
          ) : (
            <Spinner centered />
          )}
        </div>
      </Fade>
    </Modal>
  );
}

CertificatePreviewModal.propTypes = {
  show: PropTypes.any,
  onClose: PropTypes.func,
  bodyMessage: PropTypes.string.isRequired,
  background: PropTypes.string,
  footer: PropTypes.string,
  showSchedule: PropTypes.bool,
  broadcastRoomIds: PropTypes.arrayOf(PropTypes.number).isRequired,
};

CertificatePreviewModal.defaultProps = {
  show: false,
  onClose: () => {},
  background: null,
  footer: null,
  showSchedule: false,
};
