import React from "react";

// CUSTOM IMPORTS
import EmailForm from "../../components/EmailForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminEmails } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";

function CreateEmail() {
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.emails.plural"),
        function: openAdminEmails,
      },
      {
        id: 2,
        name: translate("entities.emails.new"),
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return <EmailForm />;
}

export default CreateEmail;
