import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { ScrollContainer, PartnerCheckList } from "./styles";

function NetworkRoomSponsorShip({ selectedPartners, setSelectedPartners }) {
  return (
    <ScrollContainer>
      <PartnerCheckList
        selectedPartners={selectedPartners}
        setSelectedPartners={setSelectedPartners}
      />
    </ScrollContainer>
  );
}

NetworkRoomSponsorShip.propTypes = {
  selectedPartners: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedPartners: PropTypes.func.isRequired,
};

export default NetworkRoomSponsorShip;
