import React from "react";
import PropTypes from "prop-types";

//* NAVIGATIONS
import { openMarketRoom } from "~/shared/routes/navigation";

//* APIs
import {
  createUserMeetingEvaluation,
  showUserMeetingAvarageEvaluation,
} from "../../../../apis/meetings";

//* CUSTOM IMPORTS
import { EvaluationContainer } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { getFullDate, getFullHours, isDateAfter } from "~/shared/utils/dates";
import { FooterButtons } from "~/shared/components/molecules";
import { useMySchedule } from "../../hooks";
import { useAlert } from "~/shared/hooks/alert";

const meetingDetailsPropTypes = {
  data: PropTypes.object.isRequired,
  onCancel: PropTypes.func.isRequired,
};

const MeetingDetails = ({ data, onCancel }) => {
  const { meeting, id: scheduledMeetingId } = data;
  const { partner, start_date: startDate, end_date: endDate, id } = meeting;
  const { translate } = useTranslation();
  const { cancelMyMeeting } = useMySchedule();
  const { showAlert } = useAlert();

  //* STATES
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [evaluation, setEvaluation] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    showUserMeetingAvarageEvaluation({ meetingId: id }).then(response => {
      setEvaluation(response.data.evaluation);
      setIsLoaded(true);
    });
  }, [id]);

  const handleOnEvaluate = value => {
    setEvaluation(value);

    createUserMeetingEvaluation({
      evaluation: value,
      meetingId: id,
      partnerId: partner.id,
    });
  };

  const handleOnMeetingCancel = () => {
    onCancel();
    cancelMyMeeting(scheduledMeetingId, startDate);
  };

  const showDeleteAlert = () => {
    showAlert({
      title: translate("entities.meetings.cancel"),
      variant: "error",
      onConfirm: handleOnMeetingCancel,
      description: (
        <>
          {translate("common.AreYouSureYouWantToCancel")}{" "}
          <strong>
            {translate("common.meetingWith")} {partner.title}?
          </strong>
        </>
      ),
    });
  };

  return (
    <EvaluationContainer
      hideRate={isDateAfter({ date: new Date(), dateToCompare: endDate })}
      title={`${translate("common.meetingWith")} ${partner.title}`}
      subTitle={`${getFullDate(startDate)}  |  ${getFullHours(
        startDate
      )} -${getFullHours(endDate)}`}
      rateTitle={translate("entities.meetings.evaluate")}
      onRateChange={handleOnEvaluate}
      isRateLoading={!isLoaded}
      rate={evaluation}
      onInfoClick={partnerInfo =>
        openMarketRoom(partnerInfo.id, partnerInfo.title)
      }
      infosData={[
        {
          id: partner.id,
          avatar: { src: partner.logo },
          subTitle: partner.bio,
          title: partner.title,
        },
      ]}
      footerComponent={
        <FooterButtons
          disableConfirmButton
          cancelButtonVariant="outlined"
          style={{ marginTop: 20, justifyContent: "flex-start" }}
          saveButton={translate("entities.meetings.access")}
          cancelButton={translate("entities.meetings.cancel")}
          onCancel={showDeleteAlert}
        />
      }
    />
  );
};

MeetingDetails.propTypes = meetingDetailsPropTypes;

export { MeetingDetails };
