import React from "react";
import PropTypes from "prop-types";
import {
  List,
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";
import { MdChat, MdArrowDownward } from "react-icons/md";
import { useSelector } from "react-redux";

//* CUSTOM IMPORTS
import { useSigned } from "~/modules/signed/hooks";
import { Empty } from "~/shared/components/molecules";
import { ChatMessageCell } from "~/shared/components/organisms/cells/ChatMessageCell";
import { isListValid, isDataValid } from "~/shared/utils/validations";
import ChatMessage from "../ChatMessage";
import {
  Container,
  Loader,
  QuestionButton,
  ReturnBottomButton,
} from "./styles";

const BIG_NUMBER = 1000000000;

function ChatMessageWindow({
  className,
  chatMessages,
  isLoaded,
  broadcastRoomId,
  userId,
  onQuestionButtonClick,
  onReplyClick,
  isBottomReached,
  setIsBottomReached,
}) {
  // REDUCER
  const { isMyUserAdmin } = useSigned();
  const { lastReaction } = useSelector(
    state => state.wsReactions.broadcastRoomChat
  );
  const { isSending, isDeleting, messageId } = useSelector(
    state => state.wsBroadcastRoomChat.messages
  );

  // REF
  const listRef = React.createRef();

  // STATES
  const [showReturnButton, setShowReturnButton] = React.useState(false);

  const [isListMounted, setIsListMounted] = React.useState(false);

  // FUNCTIONS
  const rowCount = chatMessages.length;

  const cache = new CellMeasurerCache({
    fixedWidth: true,
  });

  const scrollToBottom = React.useCallback(
    () =>
      isDataValid(listRef.current) && listRef.current.scrollToRow(BIG_NUMBER),
    [listRef]
  );

  React.useEffect(() => {
    // Only scroll to the last message if scroll position is in bottom
    if (isBottomReached) scrollToBottom();
  }, [isBottomReached, scrollToBottom]);

  const handleScroll = element => {
    //* Set flag if user is in bottom of the list or not
    if (element.scrollHeight - element.scrollTop === element.clientHeight) {
      setIsListMounted(true);
      setIsBottomReached(true);

      setShowReturnButton(false);
    } else {
      setIsBottomReached(false);

      //* If chat has more than 5 messages and is not on bottom display an
      //* button to return to the bottom
      if (rowCount > 5 && element.scrollTop <= element.scrollHeight / 2) {
        setShowReturnButton(true);
      } else {
        setShowReturnButton(false);
      }
    }
  };

  const isMyQuestion = chatMessage => {
    const { message, user } = chatMessage;

    if (message.includes("?") && userId === user.id) return true;

    return false;
  };

  const renderCell = (measure, index) => {
    const isLastCell = rowCount - 1 === index;

    return (
      <div
        style={{
          width: "calc(100% - 8px)",
          margin: "0 auto",
        }}
      >
        {isDeleting && messageId === chatMessages[index].id ? (
          /** Render a loader instead of the default ChatMessageCell when user delete an message */
          <ChatMessageCell
            isLoading
            style={{ margin: "0 16px 16px", width: "calc(100% - 32px)" }}
          />
        ) : (
          <ChatMessage
            style={{
              margin: "12px 14px 0",
              marginBottom: index === rowCount - 1 ? 32 : 8,
              zIndex: 1,
            }}
            isAdmin={isMyUserAdmin}
            broadcastRoomId={broadcastRoomId}
            chatMessage={chatMessages[index]}
            onMeasure={measure}
            lastReaction={lastReaction}
            userId={userId}
            onReplyClick={() => onReplyClick(chatMessages[index])}
          />
        )}

        {isSending && isLastCell && (
          /** Render a loader at the bototm of the list, when user send a new
        message */
          <ChatMessageCell
            isLoading
            style={{ margin: "0 16px 16px", width: "calc(100% - 32px)" }}
          />
        )}
        {isMyQuestion(chatMessages[index]) && (
          <QuestionButton onClick={onQuestionButtonClick}>
            Quer fazer uma pergunta ao palestrante? Utilize o botão
            <strong>Envie sua Pergunta</strong> abaixo!
          </QuestionButton>
        )}
      </div>
    );
  };

  const rowRenderer = ({ index, key, style, parent }) => {
    return (
      <CellMeasurer
        cache={cache}
        columnIndex={0}
        key={key}
        parent={parent}
        rowIndex={index}
      >
        {({ measure, registerChild }) => (
          <div ref={registerChild} style={style} key={key}>
            {renderCell(measure, index)}
          </div>
        )}
      </CellMeasurer>
    );
  };

  return isLoaded ? (
    <Container className={className}>
      {showReturnButton && (
        <ReturnBottomButton onClick={scrollToBottom} type="button">
          <MdArrowDownward />
        </ReturnBottomButton>
      )}

      {isListValid(chatMessages) ? (
        <AutoSizer>
          {({ width, height }) => (
            <List
              ref={listRef}
              id="chat-messager-list"
              rowCount={rowCount}
              height={height}
              width={width}
              rowRenderer={rowRenderer}
              rowHeight={cache.rowHeight}
              onScroll={handleScroll}
              scrollToIndex={
                isBottomReached || !isListMounted ? BIG_NUMBER : -1
              }
            />
          )}
        </AutoSizer>
      ) : (
        <Empty
          message="Sem mensagens de chat"
          description=" Começe a conversar para ver sua mensagem aqui"
          icon={<MdChat />}
          style={{ padding: 20 }}
        />
      )}
    </Container>
  ) : (
    <Loader centered />
  );
}

ChatMessageWindow.propTypes = {
  className: PropTypes.string,
  chatMessages: PropTypes.arrayOf(PropTypes.object).isRequired,
  isLoaded: PropTypes.any.isRequired,
  broadcastRoomId: PropTypes.number,
  userId: PropTypes.number.isRequired,
  onQuestionButtonClick: PropTypes.func.isRequired,
  onReplyClick: PropTypes.func.isRequired,
  isBottomReached: PropTypes.bool.isRequired,
  setIsBottomReached: PropTypes.bool.isRequired,
};

ChatMessageWindow.defaultProps = {
  className: null,
  broadcastRoomId: null,
};

export default ChatMessageWindow;
