import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listUsersSuccessful,
  createUserSuccessful,
  createUserFailed,
  showUserSuccessful,
  updateUserFailed,
  updateUserSuccessful,
  deleteUserFailed,
  deleteUserSuccessful,
  clearEvents,
  listUserActivitiesSuccessful,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list({ payload }) {
  try {
    const { page, search } = payload;

    const response = yield call(api.get, "/admin/user", {
      params: {
        page,
        search,
      },
    });

    if (validResponseStatus(response.status)) {
      const users = response.data;

      yield put(listUsersSuccessful(users));
    }
  } catch (err) {
    console.log(err);
  }
}

// SHOW
export function* show({ payload }) {
  try {
    const { userId } = payload;

    const response = yield call(api.get, `/admin/user/${userId}`);

    if (validResponseStatus(response.status)) {
      const user = response.data;

      yield put(showUserSuccessful(user));
    }
  } catch (err) {
    console.log(err);
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { user } = payload;

    const response = yield call(api.post, "/admin/user", user);

    if (validResponseStatus(response.status)) {
      const newUser = response.data;

      yield put(createUserSuccessful(newUser));
    }

    yield put(clearEvents());
  } catch (err) {
    yield put(createUserFailed());
    console.log(err);
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { userId, user } = payload;

    const response = yield call(api.put, `/admin/user/${userId}`, user);

    if (validResponseStatus(response.status)) {
      const updatedUser = response.data;

      yield put(updateUserSuccessful(updatedUser));
    }

    yield put(clearEvents());
  } catch (err) {
    yield put(updateUserFailed());
    console.log(err);
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { userId } = payload;

    const response = yield call(api.delete, `/admin/user/${userId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteUserSuccessful(userId));
    }

    yield put(clearEvents());
  } catch (err) {
    yield put(deleteUserFailed());
    console.log(err);
  }
}
// ACTIVITIES
export function* listActivities({ payload }) {
  const { userId, page } = payload;

  try {
    const response = yield call(api.get, `/admin/access_log`, {
      params: { userId, page },
    });

    if (validResponseStatus(response.status)) {
      const activities = response.data;

      yield put(
        listUserActivitiesSuccessful({ activities, page: activities.page })
      );
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.SHOW.REQUEST, show),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
  takeLatest(types.ACTIVITIES.REQUEST, listActivities),
]);
