import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Switch, Typography, Line } from "../../atoms";
import { footerButtonsDefaultProps } from "..";
import { Container, Option } from "./styles";

const switchSettingModalPropTypes = {
  /** Control the visibility of the modal */
  show: PropTypes.bool,
  /** Object (id: number, description: string, isSelected: bool) containing to be displayed */
  options: PropTypes.object,
  /** Title of the modal */
  title: PropTypes.string,
  // /** String to be rendered inside confirm button */
  // saveButton: PropTypes.string,
  // /** Callback trigered when save button is clicked */
  // onClick: PropTypes.func,
  // /** Callback trigered when cancel button is clicked */
  // onCancel: PropTypes.func,
  /** If not null, render a extra <b>Element</b> in the modal under a <b>Line</b> */
  extraContent: PropTypes.element,
  /** if True render a <b>Footer</b> in the modal, default is false */
  hasFooter: PropTypes.bool,
  /** Control all props for <b>FooterByuttons</b> component. See all props into <b>molecules/FooterButtons</b> */
  footerButtons: PropTypes.shape(footerButtonsDefaultProps),
};

const switchSettingModalDefaultPropTypes = {
  title: null,
  show: false,
  extraContent: null,
  hasFooter: false,
  options: null,
  footerButtons: null,
};

function SwitchSettingModal({
  title,
  show,
  options,
  extraContent,
  hasFooter,
  footerButtons,
  ...rest
}) {
  // STATES
  return (
    <Container
      show={show}
      title={title}
      footerButtons={footerButtons}
      hasFooter={hasFooter}
      {...rest}
    >
      {options &&
        options.length > 0 &&
        options.map(option => (
          <Option>
            <Typography>{option.description}</Typography>
            <Switch isChecked={option.isSelected} onChange={option.onChange} />
          </Option>
        ))}
      {extraContent && (
        <>
          <Line style={{ marginTop: "8px", marginBottom: "24px" }} />
          {extraContent}
        </>
      )}
    </Container>
  );
}

SwitchSettingModal.propTypes = switchSettingModalPropTypes;
SwitchSettingModal.defaultProps = switchSettingModalDefaultPropTypes;

export { SwitchSettingModal, switchSettingModalPropTypes };
