import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIS
import { listBookmarks as callListBookmarks } from "~/modules/signed/modules/client/apis/profile";

//* CUSTOM IMPORTS
import { useProfile } from "..";
import { BOOKMARK_TYPES } from "~/shared/utils/types";

const ProfileContext = createContext({});

const BOOKMARKS_INITIAL_DATA = {
  data: [],
  isLoaded: false,
  page: 1,
  lastPage: null,
  isPaging: false,
};

const ProfileBookmarkProvider = ({ children }) => {
  // STATES
  const { selectedUser } = useProfile();

  const [bookmarkRooms, setBookmarkRooms] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );
  const [bookmarkNetworkRooms, setBookmarkNetworkRooms] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );
  const [bookmarkPosters, setBookmarkPosters] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );
  const [bookmarkSchedules, setBookmarkSchedules] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );
  const [bookmarkSpeakers, setBookmarkSpeakers] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );
  const [bookmarkMarketRooms, setBookmarkMarketRooms] = React.useState(
    BOOKMARKS_INITIAL_DATA
  );

  const [roomsCurrentPage, setRoomsCurrentPage] = React.useState(1);
  const [networkRoomsCurrentPage, setNetworkRoomsCurrentPage] = React.useState(
    1
  );
  const [postersCurrentPage, setPostersCurrentPage] = React.useState(1);
  const [scheduleCurrentPage, setScheduleCurrentPage] = React.useState(1);
  const [speakersCurrentPage, setSpeakersCurrentPage] = React.useState(1);
  const [marketRoomsCurrentPage, setMarketRoomsCurrentPage] = React.useState(1);
  const [searchString, setSearchString] = React.useState("");
  const [isSearching, setIsSearching] = React.useState(false);

  // FUNCTIONS

  /** callback to populate user bookmarks */
  const listBookmarks = React.useCallback(
    (page, type, setState, search) => {
      const list = async () => {
        setIsSearching(true);

        await callListBookmarks({
          page,
          search,
          profile_id: selectedUser.id,
          type,
        }).then(response => {
          setState(() => {
            return {
              data: response.data.data,
              isLoaded: true,
              page: response.data.page,
              lastPage: response.data.lastPage,
              isPaging: false,
            };
          });
        });

        setIsSearching(false);
        setState(value => ({ ...value, isPaging: false, isSearching: false }));
      };

      list();
    },
    [selectedUser.id]
  );

  const clearBookmarks = () => {
    setBookmarkRooms(BOOKMARKS_INITIAL_DATA);
    setBookmarkNetworkRooms(BOOKMARKS_INITIAL_DATA);
    setBookmarkPosters(BOOKMARKS_INITIAL_DATA);
    setBookmarkSchedules(BOOKMARKS_INITIAL_DATA);
    setBookmarkSpeakers(BOOKMARKS_INITIAL_DATA);
    setBookmarkMarketRooms(BOOKMARKS_INITIAL_DATA);
  };

  const listMarket = React.useCallback(
    search => {
      const list = async () => {
        setIsSearching(true);

        await callListBookmarks({
          search,
          profile_id: selectedUser.id,
          type: "partners",
        }).then(response => {
          setIsSearching(false);

          setBookmarkMarketRooms(() => {
            return {
              data: response.data,
              isLoaded: true,
            };
          });
        });
      };

      list();
    },
    [selectedUser.id]
  );

  const setBookmarks = React.useCallback((status, callback) => {
    switch (status) {
      case BOOKMARK_TYPES.ROOMS:
        return setBookmarkRooms(value => callback(value));

      case BOOKMARK_TYPES.NETWORK:
        return setBookmarkNetworkRooms(value => callback(value));

      case BOOKMARK_TYPES.POSTERS:
        return setBookmarkPosters(value => callback(value));

      case BOOKMARK_TYPES.SCHEDULE:
        return setBookmarkSchedules(value => callback(value));

      case BOOKMARK_TYPES.SPEAKERS:
        return setBookmarkSpeakers(value => callback(value));

      case BOOKMARK_TYPES.MARKET:
        return setBookmarkMarketRooms(value => callback(value));

      default:
    }
  }, []);

  const setPagination = React.useCallback((type, currentPage) => {
    switch (type) {
      case BOOKMARK_TYPES.ROOMS:
        setBookmarkRooms(value => ({ ...value, isPaging: true }));
        return setRoomsCurrentPage(currentPage);

      case BOOKMARK_TYPES.NETWORK:
        setBookmarkNetworkRooms(value => ({ ...value, isPaging: true }));
        return setNetworkRoomsCurrentPage(currentPage);

      case BOOKMARK_TYPES.POSTERS:
        setBookmarkPosters(value => ({ ...value, isPaging: true }));
        return setPostersCurrentPage(currentPage);

      case BOOKMARK_TYPES.SCHEDULE:
        setBookmarkSchedules(value => ({ ...value, isPaging: true }));
        return setScheduleCurrentPage(currentPage);

      case BOOKMARK_TYPES.SPEAKERS:
        setBookmarkSpeakers(value => ({ ...value, isPaging: true }));
        return setSpeakersCurrentPage(currentPage);

      case BOOKMARK_TYPES.MARKET:
        setBookmarkMarketRooms(value => ({ ...value, isPaging: true }));
        return setMarketRoomsCurrentPage(currentPage);

      default:
    }
  }, []);

  /** Paginate control */
  const paginateBookmarks = React.useCallback(
    (type, page) => {
      setBookmarks(type, value => ({
        ...value,
        page,
      }));
      setPagination(type, page);
    },
    [setBookmarks, setPagination]
  );

  return (
    <ProfileContext.Provider
      value={{
        bookmarkRooms,
        bookmarkNetworkRooms,
        bookmarkPosters,
        bookmarkSchedules,
        bookmarkSpeakers,
        bookmarkMarketRooms,
        searchString,
        roomsCurrentPage,
        networkRoomsCurrentPage,
        postersCurrentPage,
        scheduleCurrentPage,
        speakersCurrentPage,
        marketRoomsCurrentPage,
        isSearching,
        BOOKMARKS_INITIAL_DATA,

        setBookmarkRooms,
        setBookmarkNetworkRooms,
        setBookmarkPosters,
        setBookmarkSchedules,
        setBookmarkSpeakers,
        setBookmarkMarketRooms,
        setSearchString,
        setIsSearching,
        setBookmarks,
        paginateBookmarks,
        listBookmarks,
        listMarket,
        clearBookmarks,
      }}
    >
      {children}
    </ProfileContext.Provider>
  );
};

ProfileBookmarkProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useBookmarkProfile = () => {
  const context = useContext(ProfileContext);

  if (!context)
    throw new Error(
      "useBookmarkProfile must be used within ProfileBookmarkProvider"
    );

  return context;
};

export { ProfileBookmarkProvider, useBookmarkProfile };
