import React from "react";
import PropTypes from "prop-types";
import { MdFileDownload } from "react-icons/md";

// CUSTOM IMPORTS
import {
  createPosterRate,
  showPosterRate,
} from "~/modules/signed/modules/client/apis/posters/rate";
import {
  Container,
  Header,
  Title,
  DowloadLink,
  DowloadButton,
  AuthorField,
  AuthorName,
  AccentTitle,
  SummaryBody,
  Summary,
  Footer,
  Stars,
  Comments,
  ButtonContainer,
  BookmarkButton,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function PosterDetailsFragment({ poster }) {
  const {
    id: posterId,
    title,
    author,
    co_author,
    description,
    download_url: dowloadUrl,
    bookmark,
  } = poster;

  // STATES
  const [isRateLoaded, setIsRateLoaded] = React.useState(false);
  const [rate, setRate] = React.useState(0);

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const showRate = async () => {
      await showPosterRate({ posterId }).then(response => {
        setRate(response.data.rate);

        setIsRateLoaded(true);
      });
    };

    showRate(showRate);
  }, [posterId]);

  const evaluatePoster = async rate => {
    await createPosterRate({ posterId, rate });
  };

  return (
    <Container>
      <Header>
        <Title>{title}</Title>

        <div id="poster-authors">
          <AuthorField>
            <h5>Autor</h5>
            <AuthorName>{author}</AuthorName>
          </AuthorField>

          {co_author && (
            <AuthorField>
              <h5>Co Autor</h5>
              {String(co_author)
                .split(",")
                .map(c => (
                  <AuthorName key={c}>{c}</AuthorName>
                ))}
            </AuthorField>
          )}
        </div>

        {dowloadUrl && (
          <ButtonContainer>
            <DowloadLink href={dowloadUrl} download={title} target="_blank">
              <DowloadButton
                type="button"
                variant="contained"
                title="Baixar Poster"
              >
                <MdFileDownload />
                Baixar Pôster
              </DowloadButton>
            </DowloadLink>
            <BookmarkButton
              isSelected={bookmark}
              apiType="posters"
              apiId={posterId}
            />
          </ButtonContainer>
        )}
      </Header>

      {description && (
        <SummaryBody>
          <div id="summary-div">
            <AccentTitle>Resumo</AccentTitle>
            <Summary>{description}</Summary>
          </div>
        </SummaryBody>
      )}

      <Footer>
        <Comments posterId={posterId} />

        <Stars
          isLoaded={isRateLoaded}
          title={translate("common.rateThisSession")}
          initialRate={rate}
          onClick={evaluatePoster}
        />
      </Footer>
    </Container>
  );
}

PosterDetailsFragment.propTypes = {
  poster: PropTypes.object.isRequired,
};

export default PosterDetailsFragment;
