import React from "react";
import PropTypes from "prop-types";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

const FieldsetLoader = ({ width, height, ...rest }) => {
  const { colors } = React.useContext(ThemeContext);
  return (
    <ContentLoader
      speed={2}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
      {...rest}
    >
      <rect x="0" y="0" rx="3" ry="3" width="88" height="6" />
      <rect x={width} y="17" rx="3" ry="3" width="52" height="6" />
      <rect x="0" y="18" rx="3" ry="3" width="140" height="6" />
    </ContentLoader>
  );
};

FieldsetLoader.propTypes = {
  height: PropTypes.number,
  width: PropTypes.number,
};

FieldsetLoader.defaultProps = {
  height: 40,
  width: 140,
};

export default FieldsetLoader;
