import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTSz
import { ISO_COUNTRY_CODES } from "../../../translation/constants";
import {
  Box,
  IconedButton,
  Typography,
  HoverWrapper,
  CountryIcon,
  countryIconDefaultProps,
  countryIconPropTypes,
} from "../../atoms";

import {
  SimpleMenu,
  CountryButton,
  CRUDOptionsMenu,
  LanguageButton,
} from "./styles";
import { useTranslation } from "../../../hooks/translation";

const languagesMenuPropTypes = {
  /** The array of flags to be rendered */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,

      /** This props define which flag will be rendered,
       *
       * you can see all countryCodes <a target="blank" href="https://www.iso.org/obp/ui/#search/code/">here</a>
       */
      countryCode: countryIconPropTypes.countryCode,

      /** If <b>true</b> enable <b>delete button</b> */
      isDeletable: PropTypes.bool,
    })
  ),

  /** This props define which flag will be rendered,
   *
   * you can see all countryCodes <a target="blank" href="https://www.iso.org/obp/ui/#search/code/">here</a>
   */
  buttonCountryCode: countryIconPropTypes.countryCode,

  /** Callback that is triggered when user clicks into <b>EditButton</b> of
   * an Flag, return the flag data.
   *
   * Example: (flag) => {}
   */
  onFlagEdit: PropTypes.func,

  /** Callback that is triggered when user clicks to delete an flag.
   *
   * Example: (flag) => {}
   */
  onFlagDelete: PropTypes.func,

  /** Callback that is triggered when user clicks into an Flag of
   * an Flag, return the flag data.
   *
   * Example: (flag) => {}
   */
  onFlagClick: PropTypes.func,

  /** If this props has data add an <b>AddButton</b> at top of this components,
   * Callback that is triggered when user clciks into <b>AddButton</b>
   */
  onAddFlag: PropTypes.func,

  buttonSize: countryIconPropTypes.size,

  /** Control the color of the arrow, default is theme.colors.strong */
  buttonSelectColor: PropTypes.string,

  /** if this props is <b>true</b> will ignore the <b>data</b> props,
   * and render an list with flag icons based on ISO_COUNTRY_CODES
   */
  listAllFlags: PropTypes.bool,

  /** The ID of the selected flag, will add an accent color to the selected item */
  selectedFlagId: PropTypes.number,

  /** If this props has some element replace the <b>CountryButton</b> for this props. */
  menuButton: PropTypes.element,
};

const languagesMenuDefaultProps = {
  data: [],
  buttonCountryCode: countryIconDefaultProps.countryCode,
  onAddFlag: null,
  onFlagClick: () => {},
  onFlagDelete: null,
  onFlagEdit: null,
  buttonSize: countryIconDefaultProps.size,
  listAllFlags: false,
  buttonSelectColor: null,
  selectedFlagId: null,
  menuButton: null,
};

/** This component open an <b>SimpleMenu</b> with languages */
const LanguagesMenu = ({
  data,
  listAllFlags,
  buttonCountryCode,
  buttonSize,
  buttonSelectColor,
  onFlagEdit,
  onFlagDelete,
  onFlagClick,
  onAddFlag,
  menuButton,
  selectedFlagId,
  ...rest
}) => {
  const { translate } = useTranslation();

  //* STATES
  const [show, setShow] = React.useState(false);

  const languages = listAllFlags
    ? Object.values(ISO_COUNTRY_CODES).map(locale => ({
        id: locale,
        countryCode: locale,
      }))
    : data;

  const handleOnClose = () => setShow(false);

  const handleOnEdit = flag => {
    onFlagEdit(flag);

    handleOnClose();
  };

  const handleOnClick = flag => {
    onFlagClick(flag);

    handleOnClose();
  };
  const handleOnDelete = flag => {
    onFlagDelete(flag);

    handleOnClose();
  };

  return (
    <SimpleMenu
      {...rest}
      button={
        menuButton || (
          <CountryButton buttonSelectColor={buttonSelectColor}>
            <CountryIcon countryCode={buttonCountryCode} size={buttonSize} />
          </CountryButton>
        )
      }
      controlled
      onOpen={() => setShow(true)}
      onClose={handleOnClose}
      show={show}
    >
      <Box className="language-menu-container" padding="8px" maxWidth="250px">
        {onAddFlag && (
          <IconedButton
            onClick={onAddFlag}
            style={{ marginBottom: 16, marginLeft: 8 }}
          >
            {translate("common.addLanguage")}
          </IconedButton>
        )}

        {languages.map(
          flag =>
            flag && (
              <Box position="relative">
                <HoverWrapper
                  borderRadius={8}
                  onClick={() => handleOnClick(flag)}
                  variant="background"
                  key={flag.id}
                  style={{
                    padding: 8,
                    width: listAllFlags && "fit-content",
                  }}
                >
                  <LanguageButton hasOptionsMenu={onFlagEdit && onFlagDelete}>
                    <Box className="language-button-left">
                      <CountryIcon countryCode={flag.countryCode} />

                      {flag.name && (
                        <Typography
                          maxLines={1}
                          className={`small bold ${selectedFlagId === flag.id &&
                            "accent"}`}
                          style={{ marginLeft: 8 }}
                        >
                          {flag.name}
                        </Typography>
                      )}
                    </Box>
                  </LanguageButton>
                </HoverWrapper>

                {onFlagEdit && onFlagDelete && (
                  <CRUDOptionsMenu
                    onlyEdit={!flag.isDeletable}
                    entity="idioma"
                    entityName={flag.name}
                    onDelete={() => handleOnDelete(flag)}
                    onEdit={() => handleOnEdit(flag)}
                    buttonIconSize={30}
                  />
                )}
              </Box>
            )
        )}
      </Box>
    </SimpleMenu>
  );
};

LanguagesMenu.propTypes = languagesMenuPropTypes;
LanguagesMenu.defaultProps = languagesMenuDefaultProps;

export { LanguagesMenu, languagesMenuPropTypes, languagesMenuDefaultProps };
