import React from "react";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useAlert } from "~/shared/hooks/alert";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { API_STATUS } from "~/shared/utils/apis";
import { useAdminGamification } from "../../hooks";
import { Form, Module, WrapContainer, Title, NumericInput } from "./styles";

function ScoreTab() {
  const { translate } = useTranslation();
  const { showFooterButtons, closeFooterButtons } = useCMS();
  const { showAlert } = useAlert();
  const {
    saveScoreSettings,
    scoreSettings,
    scoreApiStatus,
  } = useAdminGamification();

  //* FUNCTIONS
  React.useEffect(() => {
    showFooterButtons({
      disableCancelButton: true,
      formId: "admin-gamification-form",
      isLoading: scoreApiStatus === API_STATUS.PENDING,
    });
  }, [scoreApiStatus, showFooterButtons]);

  React.useEffect(() => {
    return () => {
      closeFooterButtons();
    };
  }, [closeFooterButtons]);

  const handleOnSubmit = data => {
    showAlert({
      title: translate("entities.gamification.scoreSettings.confirmSave"),
      description: translate(
        "entities.gamification.scoreSettings.confirmSaveDescription"
      ),
      onConfirm: () => saveScoreSettings(data),
    });
  };

  return (
    <Form
      id="admin-gamification-form"
      onSubmit={handleOnSubmit}
      initialData={scoreSettings}
    >
      {/* Common Module Points */}
      <Module>
        <Title className="subTitle bold">{translate("common.general")}</Title>
        <NumericInput
          title={translate("components.input.firstDayAccess.title")}
          name="first_access_points"
          tip={translate("components.input.firstDayAccess.tip")}
          tipPosition="bottom"
        />
      </Module>

      {/* Broadcast Rooms Module Points */}
      <Module>
        <Title className="subTitle bold">
          {translate("entities.broadcastRooms.plural")}
        </Title>

        <WrapContainer>
          <NumericInput
            name="broadcast_check_in_points"
            title={translate("components.input.broadcastCheckInPoints.title")}
            tip={translate("components.input.broadcastCheckInPoints.tip")}
          />
          <NumericInput
            name="rate_schedule_points"
            title={translate("components.input.rateSchedulePoints.title")}
            tip={translate("components.input.rateSchedulePoints.tip")}
          />
          <NumericInput
            name="rate_broadcast_points"
            title={translate("components.input.rateBroadcastPoints.title")}
            tip={translate("components.input.rateBroadcastPoints.tip")}
            tipPosition="right"
          />
          <NumericInput
            name="broadcast_button_points"
            title={translate("components.input.broadcastButtonPoints.title")}
            tip={translate("components.input.broadcastButtonPoints.tip")}
          />
        </WrapContainer>
      </Module>

      {/* Interaction Module Points */}
      <Module>
        <Title className="subTitle bold">
          {translate("common.interaction")}
        </Title>

        <WrapContainer>
          <NumericInput
            name="send_question_points"
            title={translate("components.input.sendQuestionPoints.title")}
            tip={translate("components.input.sendQuestionPoints.tip")}
          />
          <NumericInput
            name="first_chat_room_points"
            title={translate("components.input.firstChatRoomPoints.title")}
            tip={translate("components.input.firstChatRoomPoints.tip")}
          />
          <NumericInput
            name="like_message_points"
            title={translate("components.input.likeMessagePoints.title")}
            tip={translate("components.input.likeMessagePoints.tip")}
          />
          <NumericInput
            name="reply_message_points"
            title={translate("components.input.replyMessagePoints.title")}
            tip={translate("components.input.replyMessagePoints.tip")}
            tipPosition="right"
          />
          <NumericInput
            name="first_reaction_points"
            title={translate("components.input.firstReactionPoints.title")}
            tip={translate("components.input.firstReactionPoints.tip")}
            tipPosition="right"
          />
        </WrapContainer>
      </Module>

      {/* Favorites Module Points  */}
      <Module>
        <Title className="subTitle bold">{translate("common.favorites")}</Title>

        <WrapContainer>
          <NumericInput
            name="bookmark_partner_points"
            title={translate("components.input.bookmarkPartnerPoints.title")}
            tip={translate("components.input.bookmarkPartnerPoints.tip")}
          />

          <NumericInput
            name="bookmark_broadcast_points"
            title={translate("components.input.bookmarkBroadcastPoints.title")}
            tip={translate("components.input.bookmarkBroadcastPoints.tip")}
          />

          <NumericInput
            name="bookmark_schedule_points"
            title={translate("components.input.bookmarkSchedulePoints.title")}
            tip={translate("components.input.bookmarkSchedulePoints.tip")}
          />

          <NumericInput
            name="bookmark_speaker_points"
            title={translate("components.input.bookmarkSpeakerPoints.title")}
            tip={translate("components.input.bookmarkSpeakerPoints.tip")}
          />
        </WrapContainer>
      </Module>

      {/* Networking Module Points */}
      <Module>
        <Title className="subTitle bold">
          {translate("common.networking")}
        </Title>

        <WrapContainer>
          <NumericInput
            name="friend_connection_points"
            title={translate("components.input.friendConnectionPoints.title")}
            tip={translate("components.input.friendConnectionPoints.tip")}
          />
          <NumericInput
            name="first_chat_points"
            title={translate("components.input.firstChatPoints.title")}
            tip={translate("components.input.firstChatPoints.tip")}
          />
        </WrapContainer>
      </Module>

      {/* MarketRooms Module Points */}
      <Module>
        <Title className="subTitle bold">
          {translate("entities.marketRooms.booths")}
        </Title>

        <WrapContainer>
          <NumericInput
            name="partner_check_in_points"
            title={translate("components.input.partnerCheckInPoints.title")}
            tip={translate("components.input.partnerCheckInPoints.tip")}
          />
          <NumericInput
            name="partner_button_points"
            title={translate("components.input.partnerButtonPoints.title")}
            tip={translate("components.input.partnerButtonPoints.tip")}
          />

          <NumericInput
            name="partner_social_points"
            title={translate("components.input.partnerSocialPoints.title")}
            tip={translate("components.input.partnerSocialPoints.tip")}
          />
        </WrapContainer>
      </Module>
    </Form>
  );
}

export { ScoreTab };
