import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Badge as BadgeC } from "../Badge";

export const IconContainer = styled.div`
  position: relative;
  display: flex;
  background-color: ${props => props.backgroundColor};

  svg {
    height: ${props => props.size}px;
    width: ${props => props.size}px;
    min-height: ${props => props.size}px;
    min-height: ${props => props.size}px;

    color: ${props => {
      const { variant, color, theme } = props;

      if (color) return color;

      switch (variant) {
        case "correct":
          return theme.colors.greenCorrect;

        case "error":
          return theme.colors.redWrong;

        case "warning":
          return theme.colors.yellowWarning;

        case "white":
          return "#fff";

        case "black":
          return "#000";

        case "opaque":
          return theme.colors.opaque;

        case "smoked":
          return theme.colors.smoked;

        case "accent":
          return theme.colors.accent;

        case "primary":
          return theme.colors.primary;

        default:
          return theme.colors.opaque;
      }
    }};
  }

  ${props => {
    switch (props.variant) {
      case "contained":
        return css`
          border-radius: 50%;
          background-color: ${props.backgroundColor};
          padding: 12px;
          box-shadow: 0 2px 5px ${props.theme.colors.shadow};
        `;

      default:
        break;
    }
  }}
`;

export const Badge = styled(BadgeC)`
  position: absolute;
  top: 0px;
  right: 0px;
`;
