import api from "~/shared/services/api";

export const generateCertificate = ({ certificateId }) =>
  Promise.resolve(api.get(`certificate/${certificateId}`));

export const getCertificateSchedule = ({ certificateId }) =>
  Promise.resolve(
    api.get("/certificate_room", {
      params: {
        certificate_id: certificateId,
      },
    })
  );
