import React from "react";

// CUSTOM IMPORTS
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { medias } from "~/shared/styles";
import { Container, Banners, BannerForm } from "./styles";

function BannersTab() {
  // STATE
  const [isFormVisible, setIsFormVisible] = React.useState(false);
  const [editMode, setEditMode] = React.useState(false);
  const [banner, setBanner] = React.useState({});

  const { scrollToTop, scrollTop } = useCMS();

  // FUNCTION
  function handleScroll() {
    if (window.innerWidth <= medias.WIDE) {
      const containerHeigth = document.getElementById("banners-tab")
        .clientHeight;

      scrollTop(containerHeigth + 600);
    } else {
      scrollToTop();
    }
  }

  function handleShowForm(banner, mode = false) {
    handleScroll();
    setIsFormVisible(false);
    setBanner(banner);
    setEditMode(mode);

    setTimeout(() => {
      setIsFormVisible(true);
    }, 200);
  }

  function handleCloseForm() {
    setBanner(null);
    setIsFormVisible(false);
  }

  return (
    <Container id="banners-tab">
      <Banners
        onAddButton={handleShowForm}
        onEdit={banner => handleShowForm(banner, true)}
      />

      {isFormVisible && (
        <BannerForm
          banner={banner}
          onClose={handleCloseForm}
          editMode={editMode}
        />
      )}
    </Container>
  );
}

export { BannersTab };
