import React from "react";

// CUSTOM IMPORTS
import { propTypes } from "../propTypes";
import { Container, CustomCarousel, Slide } from "./styles";

function Carrousel3d({ slides, onClick, ...rest }) {
  // FUNCTIONS
  const createSlides = () => {
    const slidesMounted = slides.map(slide => (
      <Slide
        onClick={() => onClick(slide)}
        href={slide.url}
        target="_blank"
        key={slide.id}
        src={slide.image}
      />
    ));

    return slidesMounted;
  };

  return (
    <Container {...rest} hasOnlyOneBanner={slides.length === 1}>
      <CustomCarousel
        className="3d-carrousel"
        slides={createSlides()}
        autoplay
        interval={5000}
      />
    </Container>
  );
}

Carrousel3d.propTypes = propTypes;

export default Carrousel3d;
