/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import history from "~/shared/services/history";
import types from "./types";
import {
  listExtraButtonsSuccessful,
  createExtraButtonSuccessful,
  createExtraButtonFailed,
  updateExtraButtonFailed,
  updateExtraButtonSuccessful,
  deleteExtraButtonFailed,
  deleteExtraButtonSuccessful,
  listExtraPlayerSuccessful,
  createExtraPlayerSuccessful,
  createExtraPlayerFailed,
  updateExtraPlayerSuccessful,
  updateExtraPlayerFailed,
  deleteExtraPlayerSuccessful,
  deleteExtraPlayerFailed,
  createExtraEmbedSuccessful,
  createExtraEmbedFailed,
  updateExtraEmbedSuccessful,
  updateExtraEmbedFailed,
  listExtraEmbedsSuccessful,
  listExtraEmbedsFailed,
  deleteExtraEmbedSuccessful,
  deleteExtraEmbedFailed,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";
import { EXTRA_EMBED_TYPES } from "~/shared/utils/types";

// EXTRA EMBEDS
// LIST
export function* listExtraEmbeds({ payload }) {
  const { refId, refType } = payload;

  let params;

  if (refType === EXTRA_EMBED_TYPES.MARKET_ROOM) params = { partner_id: refId };

  if (refType === EXTRA_EMBED_TYPES.BROADCAST_ROOM)
    params = { broadcast_room_id: refId };

  try {
    const response = yield call(api.get, `/admin/embed`, {
      params,
    });

    if (validResponseStatus(response.status)) {
      const extraEmbeds = response.data;

      yield put(listExtraEmbedsSuccessful({ extraEmbeds }));
    }
  } catch (err) {
    yield put(listExtraEmbedsFailed());
  }
}

// CREATE
export function* createExtraEmbed({ payload }) {
  try {
    const { extraEmbed } = payload;

    const response = yield call(api.post, "/admin/embed", extraEmbed);

    if (validResponseStatus(response.status)) {
      const newExtraEmbed = response.data;

      yield put(createExtraEmbedSuccessful(newExtraEmbed));
    }
  } catch (err) {
    yield put(createExtraEmbedFailed());
    console.log(err);
  }
}

// UPDATE
export function* updateExtraEmbed({ payload }) {
  try {
    const { extraEmbed, extraEmbedId } = payload;

    const response = yield call(
      api.put,
      `/admin/embed/${extraEmbedId}`,
      extraEmbed
    );
    if (validResponseStatus(response.status)) {
      const updatedExtraEmbed = response.data;

      yield put(updateExtraEmbedSuccessful(updatedExtraEmbed, extraEmbedId));
    }
  } catch (err) {
    yield put(updateExtraEmbedFailed());
    console.log(err);
  }
}

// DELETE
export function* deleteExtraEmbed({ payload }) {
  try {
    const { extraEmbedId } = payload;

    const response = yield call(api.delete, `/admin/embed/${extraEmbedId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteExtraEmbedSuccessful(extraEmbedId));
    }
  } catch (err) {
    yield put(deleteExtraEmbedFailed());
    console.log(err);
  }
}

// - -

