import React from "react";
import { useSelector, useDispatch } from "react-redux";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { setTagIds } from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";
import { useTranslation } from "~/shared/hooks/translation";
import {
  ScrollContainer,
  LeftContainer,
  Switch,
  RigthContainer,
  Input,
  TranslateInput,
  PlayerInput,
  FileInput,
  Tags,
} from "./styles";

function BroadcastRoomInfos({ broadcastRoom, chatControls, questionControls }) {
  const { translate } = useTranslation();

  // REDUCER
  const dispatch = useDispatch();
  const object = useSelector(state => state.adminBroadcastRooms.object);
  const { tagIds } = object;

  // STATES
  const { hasChat, setHasChat } = chatControls;
  const { hasQuestion, setHasQuestion } = questionControls;

  // FUNCTIONS
  function fillTagIds(tags) {
    dispatch(setTagIds(tags));
  }

  // Tag type, determine which type of tags wil be listed
  const TAG_TYPE = "broadcastRoom";

  return (
    <ScrollContainer id="broadcast-room-infos">
      <LeftContainer>
        <TranslateInput
          title={translate("entities.broadcastRooms.inputTitleRoom")}
          name="title"
          type="text"
          placeholder={translate(
            "entities.broadcastRooms.inputPlaceholderRoom"
          )}
        />

        <TranslateInput
          title={translate("entities.broadcastRooms.inputTitleDescriptionRoom")}
          name="description"
          type="text"
          isTextArea
          placeholder={translate(
            "entities.broadcastRooms.inputPlaholderDescriptionRoom"
          )}
        />

        <div id="broadcast-room-times">
          <Input
            title={translate("entities.broadcastRooms.inputTitleStartDate")}
            name="initial_date"
            type="date"
          />

          <Input
            title={translate("entities.broadcastRooms.inputTitleStartTime")}
            className="time-input"
            name="start_time"
            type="time"
          />

          <Input
            title={translate("entities.broadcastRooms.inputTitleEndTime")}
            name="end_time"
            type="time"
          />
        </div>

        <Switch
          title={translate("entities.broadcastRooms.switchTitleView")}
          isChecked={hasChat}
          onChange={() => setHasChat(!hasChat)}
        />

        <Switch
          title={translate("entities.broadcastRooms.switchTitleButton")}
          isChecked={hasQuestion}
          onChange={() => setHasQuestion(!hasQuestion)}
        />

        <Tags
          setSelectedTags={fillTagIds}
          selectedTags={tagIds}
          type={TAG_TYPE}
        />
      </LeftContainer>

      <RigthContainer>
        <PlayerInput
          name="video_player"
          title={translate("entities.broadcastRooms.inputTitleEmbedLink")}
          placeholder={translate(
            "entities.broadcastRooms.inputPlayerPlaceholder"
          )}
          src={isDataValid(broadcastRoom) ? broadcastRoom.video_player : null}
          type={isDataValid(broadcastRoom) ? broadcastRoom.type : "vimeo"}
        />

        <FileInput
          name="logo"
          title="Thumbnail"
          initialValue={
            isDataValid(broadcastRoom) ? broadcastRoom.room.logo : null
          }
        />
      </RigthContainer>
    </ScrollContainer>
  );
}

BroadcastRoomInfos.propTypes = {
  broadcastRoom: PropTypes.object,
  chatControls: PropTypes.object.isRequired,
  questionControls: PropTypes.object.isRequired,
};

BroadcastRoomInfos.defaultProps = {
  broadcastRoom: null,
};

export default BroadcastRoomInfos;
