import React from "react";
import { useParams } from "react-router-dom";

import FolderMaterialsList from "../FolderDetailList";
import { Typography } from "~/shared/components/atoms";
import { isDataValid } from "~/shared/utils/validations";
import { BreadCrumbs } from "~/shared/components/molecules";
import {
  UserDownloadMaterialsProvider,
  useDownloadMaterial,
} from "../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";

import { Container } from "./styles";

function FolderDetails() {
  // GET FUNCTION INSIDE ADMIN LAYOUT
  const { folderId } = useParams();
  const {
    showFolder,
    selectedFolderPath,
    setMaterialFolder,
    folderPaths,
    renderFolderPaths,
    folderTitle,
    setFolderTitle,
  } = useDownloadMaterial();
  const { translate } = useTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderId)) {
      showFolder(folderId);
    }

    return () => {
      isMounted = false;
      setMaterialFolder(null);
      setFolderTitle(null);
    };
  }, [folderId, setFolderTitle, setMaterialFolder, showFolder]);

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderTitle)) {
      renderFolderPaths(folderId, folderTitle);
    }

    return () => {
      isMounted = false;
      setFolderTitle(null);
    };
  }, [folderId, folderTitle, renderFolderPaths, setFolderTitle]);

  return (
    <Container>
      <Typography
        className="title bold accent"
        style={{ marginTop: 30, marginBottom: 20 }}
      >
        {translate("entities.downloadMaterials.downloadableMaterials")}
      </Typography>
      <BreadCrumbs
        data={folderPaths}
        selectedCrumb={selectedFolderPath}
        style={{ marginBottom: 20 }}
      />
      <FolderMaterialsList />
    </Container>
  );
}

const DownloadMaterialList = () => {
  return (
    <UserDownloadMaterialsProvider>
      <FolderDetails />
    </UserDownloadMaterialsProvider>
  );
};

export default DownloadMaterialList;
