import produce from "immer";

//* CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import reactionTypes from "./types";

const INITIAL_STATE = {
  broadcastRoom: {
    lastReaction: null,
  },

  broadcastRoomChat: {
    lastReaction: null,
  },
};

export default function assetsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case reactionTypes.channel.broadcastRoom.RECEIVE_REACTION: {
        const { reaction } = action.payload;

        if (isDataValid(reaction)) draft.broadcastRoom.lastReaction = reaction;

        break;
      }

      case reactionTypes.channel.broadcastRoomChat.RECEIVE_REACTION: {
        const { reaction } = action.payload;

        if (isDataValid(reaction)) draft.broadcastRoom.lastReaction = reaction;

        break;
      }

      case "@auth/SIGN_OUT": {
        break;
      }
      default:
    }
  });
}
