import produce from "immer";
import { isDataValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    updateStatus: null,
  },
};

export default function adminSpeakerReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.listSpeakers.SUCCESSFUL: {
        const { speakers } = action.payload;

        if (isDataValid(speakers)) draft.list.data = speakers;

        draft.list.isLoaded = true;

        break;
      }

      // CREATE
      case types.createSpeaker.REQUEST:
      case types.updateSpeaker.REQUEST:
      case types.deleteSpeaker.REQUEST: {
        draft.list.updateStatus = "PENDING";

        break;
      }
      case types.createSpeaker.SUCCESSFUL: {
        const { speaker } = action.payload;

        if (isDataValid(speaker))
          draft.list.data = [speaker, ...draft.list.data];

        draft.list.updateStatus = "FINISH";

        break;
      }
      case types.createSpeaker.FAILED:
      case types.updateSpeaker.FAILED:
      case types.deleteSpeaker.FAILED: {
        draft.list.updateStatus = "FAILED";

        break;
      }

      // UPDATE
      case types.updateSpeaker.SUCCESSFUL: {
        const { speaker } = action.payload;

        if (isDataValid(speaker)) {
          const speakers = draft.list.data;
          const speakerIndex = speakers.findIndex(s => s.id === speaker.id);

          speakers[speakerIndex] = speaker;

          draft.list.data = speakers;
        }

        draft.list.updateStatus = "FINISH";

        break;
      }

      // DELETE
      case types.deleteSpeaker.SUCCESSFUL: {
        const { speakerId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== speakerId
        );

        draft.list.data = filteredArray;
        draft.list.updateStatus = "FINISH";

        break;
      }

      // CLEAR
      case types.clear.REQUEST: {
        draft.list.updateStatus = null;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isLoaded = false;
        break;
      }
      default:
    }
  });
}
