import api from "~/shared/services/api";
import { createDate } from "~/shared/utils/dates";

export const reorderBroadcastRooms = ({ reorderedBroadcastRooms }) =>
  Promise.resolve(
    api.put(`/admin/order/broadcast_rooms`, {
      data: reorderedBroadcastRooms,
    })
  );

export const listBroadcastCategories = ({ page = 1, search = "" }) =>
  Promise.resolve(
    api.get("/admin/category_broadcastroom", {
      params: {
        page,
        search,
      },
    })
  );

export const createBroadcastRoom = ({ broadcastRoomData }) => {
  const { end_time, initial_date, start_time } = broadcastRoomData;

  const open_live = createDate({ date: initial_date, time: start_time });
  const end_live = createDate({ date: initial_date, time: end_time });

  const body = {
    open_live,
    end_live,
    ...broadcastRoomData,
  };

  return Promise.resolve(api.post("admin/broadcastroom", body));
};

export const updateBroadcastRoom = ({ broadcastRoomData, broadcastRoomId }) => {
  const { end_time, initial_date, start_time } = broadcastRoomData;

  const open_live = createDate({ date: initial_date, time: start_time });
  const end_live = createDate({ date: initial_date, time: end_time });

  const body = {
    open_live,
    end_live,
    ...broadcastRoomData,
  };

  return Promise.resolve(
    api.put(`admin/broadcastroom/${broadcastRoomId}`, body)
  );
};

export const deleteBroadcastRoom = broadcastRoomId =>
  Promise.resolve(api.delete(`admin/broadcastroom/${broadcastRoomId}`));

export const listBroadcastRooms = ({ search = "", page = 1, categoryId }) =>
  Promise.resolve(
    api.get("admin/broadcastroom", {
      params: {
        search,
        page,
        category_id: categoryId,
      },
    })
  );

export const getBroadcastRoomsCountHours = () =>
  Promise.resolve(api.get("/admin/count_hours/broadcastroom"));

export const getBroadcastRoomsReportCount = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/broadcastroom/report/count", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );

export const getBroadcastRoomsReportCountAccess = ({
  startDate,
  endDate,
  page = 1,
  perPage,
  search,
}) =>
  Promise.resolve(
    api.get("/admin/broadcastroom/report/count_access", {
      params: {
        start_date: startDate,
        end_date: endDate,
        page,
        per_page: perPage,
        search,
      },
    })
  );

export const updateCategoryBroadcastroom = (broadcastRoomId, categoryId) => {
  api.patch(`/admin/broadcastroom/category/${broadcastRoomId}`, {
    category_id: categoryId,
  });
};
