import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Icon, iconPropTypes, Typography } from "../../../atoms";

import {
  Touchable,
  Options,
  Card,
  Content,
  PrivateOverlayIcon,
  PrivateOverlay,
} from "./styles";

const folderMaterialCellPropTypes = {
  title: PropTypes.string.isRequired,

  /** If <b>true</b> add an <b>Options</b> to the component. */
  isAdmin: PropTypes.bool,

  /** If <b>true</b> add an <b>Overlay</b> with an 'block' icon. */
  isBlocked: PropTypes.bool,

  /** Callback that is triggered when user click into <b>EditButton</b>. */
  onEdit: PropTypes.func,

  /** Callback that is triggered when user click into <b>DeleteButton</b>. */
  onDelete: PropTypes.func,

  /** Callback that is triggered when user click into Container. */
  onClick: PropTypes.func,

  /** Control the icon that will ve rendered. To see all iconNames check the <b>shared/utils/IconUtils</b> */
  iconName: iconPropTypes.iconName,

  entity: PropTypes.string,
};

const folderMaterialCellDefaultProps = {
  isAdmin: false,
  isBlocked: false,
  onEdit: () => {},
  onDelete: () => {},
  onClick: () => {},
  iconName: "folder",
  entity: "Pasta",
};

/** This component is commonly used to display folders. */
const FolderMaterialCell = ({
  title,
  isAdmin,
  isBlocked,
  onEdit,
  onDelete,
  onClick,
  iconName,
  entity,
  ...rest
}) => {
  return (
    <Touchable>
      <Card {...rest}>
        {isBlocked && <PrivateOverlay />}

        {isBlocked ? (
          <PrivateOverlayIcon>
            <Icon iconName="lock" />
          </PrivateOverlayIcon>
        ) : (
          <Content onClick={onClick}>
            <Icon iconName={iconName} size={12} variant="opaque" />

            <Typography
              className="subTitle bold"
              maxLines={1}
              style={{ paddingLeft: "16px" }}
            >
              {title}
            </Typography>
          </Content>
        )}

        {isAdmin && (
          <Options
            className="options"
            entity={entity}
            entityName={title}
            onEdit={onEdit}
            onDelete={onDelete}
          />
        )}
      </Card>
    </Touchable>
  );
};

FolderMaterialCell.propTypes = folderMaterialCellPropTypes;

FolderMaterialCell.defaultProps = folderMaterialCellDefaultProps;

export { FolderMaterialCell, folderMaterialCellPropTypes };
