import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS

import {
  SearchBar,
  Container,
  RigthContainer,
  LeftContainer,
  ScheduleDetails,
  ScheduleDetailsMobile,
  ScheduleDetailsScroll,
} from "./styles";
import { Icon, Typography } from "~/shared/components/atoms";
import { useWindow } from "~/shared/hooks/window";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";
import CheckListCellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";
import { ScheduleCell } from "~/shared/components/organisms/cells";
import { getFullDateBR, getFullHours } from "~/shared/utils/dates";

function Schedule({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkSchedules,
    isSearching,
    scheduleCurrentPage,
    searchString,
    paginateBookmarks,
    listBookmarks,
    setBookmarkSchedules,
    setSearchString,
  } = useBookmarkProfile();
  const { translate } = useTranslation();
  const { windowWidth } = useWindow();
  const [showDetails, setShowDetails] = React.useState(false);
  const [selectedSchedule, setSelectedSchedule] = React.useState(null);

  React.useEffect(() => {
    if (!bookmarkSchedules.isLoaded && isFetcheable)
      listBookmarks(bookmarkSchedules.page, "schedules", setBookmarkSchedules);
  }, [
    bookmarkSchedules.isLoaded,
    bookmarkSchedules.page,
    isFetcheable,
    listBookmarks,
    setBookmarkSchedules,
  ]);

  // FUNCTIONS

  const changeDetails = schedule => {
    setSelectedSchedule(null);
    setTimeout(() => setSelectedSchedule(schedule), 1);
  };

  const openDetails = schedule => {
    setSelectedSchedule(schedule);
    setShowDetails(true);
  };

  const closeDetails = () => {
    setSelectedSchedule(null);
    setShowDetails(false);
  };

  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="date" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="date" />}
      />
    );
  };

  const renderCell = schedule => {
    const {
      title: scheduleTitle,
      speakers,
      start_time,
      end_time,
      id,
      bookmark,
    } = schedule;

    return (
      <div className="schedule-cell-container" style={{ padding: 4 }}>
        <Typography className="small opaque bold" style={{ marginBottom: 10 }}>
          {getFullDateBR(start_time)}
        </Typography>
        <ScheduleCell
          id={id}
          bookmark={bookmark}
          className="schedule-cell"
          disableMenu
          customButton={
            <BookmarkButton
              isSelected={schedule.bookmark}
              apiType="schedules"
              apiId={schedule.id}
              variant="icon"
            />
          }
          isLive={false}
          title={scheduleTitle}
          speakers={speakers}
          startTime={getFullHours(start_time)}
          endTime={getFullHours(end_time)}
          style={{ marginBottom: 0 }}
          onClick={() => {
            if (schedule !== selectedSchedule) {
              openDetails(schedule);
            } else if (schedule.id === selectedSchedule.id) {
              closeDetails(null);
            } else {
              changeDetails(schedule);
            }
          }}
        />
        {selectedSchedule === schedule && (
          <ScheduleDetailsMobile
            schedule={selectedSchedule}
            videoPlayer={{
              muted: true,
              autoPlay: true,
              plyrId: `schedulePlayer${selectedSchedule &&
                selectedSchedule.id}`,
              src:
                selectedSchedule && selectedSchedule.broadcastRoom.video_player,
              type: selectedSchedule && selectedSchedule.broadcastRoom.type,
            }}
          />
        )}
      </div>
    );
  };

  return (
    <Container style={{ display: "flex" }}>
      <LeftContainer
        style={windowWidth >= 600 ? { width: "40%" } : { width: "100%" }}
      >
        <SearchBar
          placeholder={translate("common.search")}
          onChange={value => {
            setSearchString(value);
            listBookmarks(
              bookmarkSchedules.page,
              "schedules",
              setBookmarkSchedules,
              value
            );
          }}
        />
        <CellList
          orientation="column"
          isLoaded={isUserLoaded && !bookmarkSchedules.isPaging && !isSearching}
          renderCellLoader={() => (
            <CheckListCellLoader
              avatarSize={68}
              height={68}
              infosX={90}
              style={{ marginRight: "160px", marginBottom: "32px" }}
            />
          )}
          loaderListSize={3}
          gap={20}
          data={bookmarkSchedules.data}
          renderCell={schedule => renderCell(schedule)}
          emptyComponent={handleEmptyMessage()}
        />
      </LeftContainer>

      <RigthContainer>
        {showDetails && selectedSchedule && (
          <ScheduleDetailsScroll className="min-width">
            <ScheduleDetails
              schedule={selectedSchedule}
              videoPlayer={{
                muted: true,
                autoPlay: true,
                plyrId: "schedulePlayer",
                src:
                  selectedSchedule &&
                  selectedSchedule.broadcastRoom.video_player,
                type: selectedSchedule && selectedSchedule.broadcastRoom.type,
                onLive: selectedSchedule.broadcastRoom.on_live,
              }}
            />
          </ScheduleDetailsScroll>
        )}
      </RigthContainer>

      <Pagination
        style={{ marginTop: 16 }}
        currentPage={scheduleCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.NETWORK, page);
        }}
        lastPage={bookmarkSchedules.lastPage}
      />
    </Container>
  );
}

Schedule.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { Schedule };
