import styled, { css } from "styled-components";
import MaterialTabs from "@material-ui/core/Tabs";
import MaterialTab from "@material-ui/core/Tab";
import SwipeableViews from "react-swipeable-views";

const renderTabFont = size => {
  if (size === "small") return "12px";

  return "14px";
};

export const Container = styled.div`
  .MuiTab-wrapper {
    font-size: ${props => renderTabFont(props.size)};
    font-weight: bold;
    text-transform: none;
    color: ${props =>
      props.isErrored
        ? props.theme.colors.redWrong
        : props.theme.colors.strong};

    &::before {
      display: ${props => (props.isErrored ? "flex" : "none")};
      content: " ";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: ${props => props.theme.colors.redWrong};
    }
  }

  ${props =>
    props.disableTabs &&
    css`
      margin: 0;
    `}
`;

export const SwipeViews = styled(SwipeableViews)`
  padding-top: 16px;
  overflow: visible;

  ${props =>
    props.disableTabs &&
    css`
      padding: 0;
    `}
`;

export const StaticContainer = styled.div`
  margin-top: 24px;
`;

export const SwipeView = styled.div``;

export const Tabs = styled(MaterialTabs)`
  height: 30px;
  width: 100%;

  .MuiTabs-flexContainer {
    justify-content: center;
  }

  [class*="MuiTabs-indicator"] {
    background-color: ${props => props.theme.colors.accent};
    height: 5px;
  }

  .MuiTab-root {
    width: 50%;
    min-width: 0px;
  }

  ${props =>
    props.alignment === "left" &&
    css`
  .tabs {
    width: unset;
    display: flex;
  }

  .react-swipeable-view-container {
    width: 101%;
  }

  .MuiTabs-flexContainer {
    justify-content: left;
  }


  .swipeViews {
    margin-top: 40px;
    padding-bottom: 20px;
  }

  .MuiTab-root {
    display: flex;
    width: unset;
    padding: 0 16px;
  }

  [class*="MuiTabs-indicator"] {
    background-color: ${props.theme.colors.accent};
    height: 3px;
  `}
`;

export const Tab = styled(MaterialTab)`
  .MuiTab-wrapper {
    color: ${props =>
      props.isErrored
        ? props.theme.colors.redWrong
        : props.theme.colors.strong};

    &::before {
      display: ${props => (props.isErrored ? "flex" : "none")};
      content: " ";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: ${props => props.theme.colors.redWrong};
    }
  }
`;
