import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listNotifications,
  deleteNotification,
  paginateNotifications,
  clearNotifications,
} from "~/modules/signed/modules/cms/modules/admin/store/notifications/actions";

//* CUSTOM IMPORTS
import {
  getFullDateBR,
  getFullHours,
  getDistanceToNow,
  isDateBefore,
} from "~/shared/utils/dates";
import { CRUDModal } from "~/shared/components/organisms";
import {
  OutlinedButton,
  Icon,
  Typography,
  Tag,
} from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import {
  CellList,
  SimpleCell,
  DialogModal,
} from "~/shared/components/molecules";
import CardLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { sendNotification } from "~/modules/signed/modules/cms/modules/admin/apis/notifications";

export default function NotificationsList({
  isAddButtonSelected,
  selectedNotification,
  onEdit,
  onDelete,
  onAddButton,
  ...rest
}) {
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);

  const dispatch = useDispatch();
  const {
    data: notifications,
    page,
    lastPage,
    isLoaded,
    isPaging,
    isDeleting,
  } = useSelector(state => state.adminNotifications.list);

  //* STATES
  const [notificationId, setNotificationId] = React.useState(null);
  const [notificationData, setNotificationData] = React.useState(null);
  const [showModal, setShowModal] = React.useState(false);

  //* FUNCTIONS

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listNotifications({ page }));

    return () => {
      isMounted = false;
    };
  }, [dispatch, page]);

  React.useEffect(() => {
    return () => {
      dispatch(clearNotifications());
    };
  }, [dispatch]);

  const renderDate = (sendedAt, scheduleDate) => {
    if (
      scheduleDate &&
      !isDateBefore({
        date: new Date(),
        dateToCompare: scheduleDate,
      })
    )
      return (
        <Typography className="small accent bold" style={{ marginBottom: 8 }}>
          Programada para {getFullDateBR(scheduleDate)} as{" "}
          {getFullHours(scheduleDate)}
        </Typography>
      );

    if (sendedAt)
      return (
        <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
          {`${translate("common.sent")} ${getDistanceToNow(sendedAt)}`}
        </Typography>
      );

    return (
      <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
        {`${translate("common.notificationSendNow")}`}
      </Typography>
    );
  };

  const handleOnAdd = () => {
    onAddButton();
  };

  const handleOnEdit = notification => {
    onEdit(notification);
  };

  const onDeleteNotification = id => {
    setNotificationId(id);
    dispatch(deleteNotification(id));

    setNotificationData(null);
    onDelete();
  };

  const handleSendNow = id => {
    sendNotification(id);

    setShowModal(false);
  };

  const fillItems = notification => {
    const items = [
      {
        id: 1,
        name: translate("common.sendNotification"),
        function: () => {
          setShowModal(true);
          setNotificationId(notification.id);
        },
        icon: <Icon iconName="send" />,
      },
      {
        id: 2,
        name: translate("common.editNotification"),
        function: () => handleOnEdit(notification),
        icon: <Icon iconName="edit" />,
      },
      {
        id: 3,
        name: translate("common.deleteNotification"),
        function: () => setNotificationData(notification),
        icon: <Icon iconName="delete" />,
        color: colors.redWrong,
      },
    ];

    return items;
  };

  const renderCell = notification => {
    const {
      id,
      schedule_date: scheduleDate,
      sended_at: sendedAt,
      send_now: sendNow,
      description,
      title,
      repeat,
    } = notification;

    return (
      <SimpleCell
        variant={selectedNotification === id && "accent"}
        onClick={() => handleOnEdit(notification)}
        simpleMenu={{
          fillItems: () => fillItems(notification),
        }}
      >
        <div style={{ display: "flex", marginBottom: 8 }}>
          {repeat && (
            <Tag borderRadius={4} color="#539DC2" withBoxShadow>
              A repetir
            </Tag>
          )}
          {scheduleDate && sendedAt && (
            <Tag borderRadius={4} color="#7AC051" withBoxShadow>
              Enviada
            </Tag>
          )}

          {sendNow && (
            <Tag borderRadius={4} color="#7AC051" withBoxShadow>
              Enviada
            </Tag>
          )}
          {scheduleDate &&
            !isDateBefore({
              date: new Date(),
              dateToCompare: scheduleDate,
            }) && (
              <Tag borderRadius={4} color="#ffcc00" withBoxShadow>
                Agendada
              </Tag>
            )}
        </div>
        {renderDate(sendedAt, scheduleDate)}

        <Typography className="subTitle bold" maxLines={2}>
          {title}
        </Typography>

        <Typography className="opaque" maxLines={3}>
          {description}
        </Typography>
      </SimpleCell>
    );
  };

  const handlePagination = () =>
    page < lastPage && dispatch(paginateNotifications());

  return (
    <>
      <CellList
        {...rest}
        isPaging={isPaging}
        withScroll
        noScrollReposition
        onBottomReached={handlePagination}
        scrollStyle={{ padding: 16 }}
        orientation="column"
        cellId={notificationId}
        isDeleting={isDeleting}
        isLoaded={isLoaded}
        loaderListSize={9}
        data={notifications}
        gap={20}
        firstCell={
          <OutlinedButton
            isActive={isAddButtonSelected}
            justifyContent="flex-start"
            type="button"
            direction="row"
            onClick={handleOnAdd}
            placeholder={translate("entities.notifications.add")}
            style={{ width: "100%", height: 74 }}
          />
        }
        renderCellLoader={(_, index) => (
          <CardLoader
            width={400}
            height={index === 0 ? 74 : 123}
            style={{ height: index === 0 ? 74 : 123, width: "100%" }}
          />
        )}
        renderCell={notification => renderCell(notification)}
      />

      {notificationData && (
        <CRUDModal
          show={!!notificationData}
          entity={translate("entities.notifications.plural")}
          entityName={notificationData.title}
          onClose={() => setNotificationData(null)}
          onConfirm={() => onDeleteNotification(notificationData.id)}
          type="delete"
        />
      )}

      <DialogModal
        show={showModal}
        title={translate("common.sendNotification")}
        description={translate("common.dialogNotification")}
        onConfirm={() => handleSendNow(notificationId)}
        onCancel={() => setShowModal(false)}
      />
    </>
  );
}

NotificationsList.propTypes = {
  onEdit: PropTypes.func.isRequired,
  onAddButton: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  isAddButtonSelected: PropTypes.bool.isRequired,
  selectedNotification: PropTypes.number.isRequired,
};
