import React from "react";
import { ThemeContext } from "styled-components";
import PropTypes from "prop-types";

import "emoji-mart/css/emoji-mart.css";
import { Picker } from "emoji-mart";
import { Container } from "./styles";

import i18n from "../../../translation/i18n";

const emojiPickerPropTypes = {
  /** Display preview section */
  showPreview: PropTypes.bool,

  /** Display skin tones picker. Disable both this and showPreview to remove the footer entirely. */
  showSkinTones: PropTypes.bool,

  /** The emoji set: 'apple', 'google', 'twitter', 'facebook' */
  set: PropTypes.string,

  /** An object containing localized strings */
  titles: PropTypes.object,

  /** Params: (emoji, event) => {}. */
  onClick: PropTypes.func,

  /** Style: {{ width: 330px }}. */
  style: PropTypes.object,

  /** Emoji skin: {1, 2, 3, 4, 5} */
  skin: PropTypes.number,

  /** Define color effects of the EmojiPicker, default color is
   * accent */

  color: PropTypes.string,
};

const emojiPickerDefaultProps = {
  set: "apple",
  showPreview: false,
  showSkinTones: false,
  skin: 1,
  titles: {
    search: i18n.t("common.search"),
    categories: {
      search: i18n.t("common.searchResults"),
      recent: i18n.t("common.recent"),
      smileys: i18n.t("common.smileys"),
      people: i18n.t("common.people"),
      nature: i18n.t("common.nature"),
      foods: i18n.t("common.foods"),
      activity: i18n.t("common.activity"),
      places: i18n.t("common.places"),
      objects: i18n.t("common.objects"),
      symbols: i18n.t("common.symbols"),
      flags: i18n.t("common.flags"),
      custom: i18n.t("common.custom"),
    },
  },
  onClick: () => {},
  style: null,
  color: null,
};

/** EmojiPicks is a component that accepts the use emojis in chat.
 *
 * EmojiPicker use <b>emoji-mart</b> lib, see Documentation here: <b>https://www.npmjs.com/package/emoji-mart</b>
 */
function EmojiPicker({
  onClick,
  style,
  showPreview,
  showSkinTones,
  set,
  titles,
  skin,
  color,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);

  return (
    <Container {...rest}>
      <Picker
        color={color || colors.accent}
        onClick={onClick}
        style={style}
        showPreview={showPreview}
        showSkinTones={showSkinTones}
        set={set}
        i18n={titles}
        skin={skin}
      />
    </Container>
  );
}

EmojiPicker.propTypes = emojiPickerPropTypes;

EmojiPicker.defaultProps = emojiPickerDefaultProps;

export { EmojiPicker, emojiPickerPropTypes, emojiPickerDefaultProps };
