import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { DialogModal } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

const disconnectModalPropTypes = {
  user: PropTypes.object.isRequired,
};

const disconnectModalDefaultPropTypes = {};

function DisconnectModal({ user, ...rest }) {
  const { translate } = useTranslation();
  return (
    <DialogModal
      {...rest}
      variant="error"
      confirmButton={translate("entities.profile.header.disconnect")}
      title={translate("entities.profile.header.disconnect")}
      description={`${translate(
        "entities.profile.body.connection.disconnectMessage"
      )} ${user.name}? ${translate(
        "entities.profile.body.connection.disconnectMessage2"
      )}`}
    />
  );
}

DisconnectModal.propTypes = disconnectModalPropTypes;
DisconnectModal.defaultProps = disconnectModalDefaultPropTypes;

export default DisconnectModal;
