import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography, Trans, Icon } from "~/shared/components/atoms";
import { PartnerCell } from "~/shared/components/organisms/cells";
import { openMarketRoom } from "~/shared/routes/navigation";
import { CellList, Empty } from "./styles";

function MarketRoomViewList({
  isLoaded,
  partners,
  name,
  level,
  isLastOne,
  search,
  renderCustomButton,
}) {
  //* FUNCTION
  const renderCell = partner => {
    const {
      id,
      title,
      sub_title: subTitle,
      logo,
      tags,
      bio,
      list_wallpaper: wallpaper,
      bookmark,
    } = partner && partner;

    return (
      <PartnerCell
        key={id}
        variant={level}
        title={title}
        banner={wallpaper}
        subTitle={subTitle}
        bio={bio}
        avatar={logo}
        tags={tags}
        customButton={renderCustomButton(id, bookmark)}
        onClick={() => openMarketRoom(partner.id, partner.title)}
      />
    );
  };

  return (
    <CellList
      isLastOne={isLastOne}
      isLoaded={isLoaded}
      data={partners}
      headerComponent={
        <Typography className="bold title accent" style={{ marginBottom: 20 }}>
          {name}
        </Typography>
      }
      renderCell={partner => renderCell(partner)}
      gap={20}
      listStyle={{ marginBottom: 40 }}
      emptyComponent={
        <Empty
          icon={<Icon iconName="marketRooms" />}
          variant={search && "search"}
          search={search}
          message={
            <Trans translationKey="entities.marketRooms.empty.message">
              Sem Estandes Virtuais
            </Trans>
          }
          description={
            <Trans translationKey="entities.marketRooms.empty.description">
              Ainda não há nenhum Estande Virtual cadastrado
            </Trans>
          }
        />
      }
    />
  );
}

MarketRoomViewList.propTypes = {
  partners: PropTypes.arrayOf(PropTypes.object).isRequired,
  name: PropTypes.string.isRequired,
  level: PropTypes.string.isRequired,
  isLastOne: PropTypes.bool.isRequired,
  search: PropTypes.string.isRequired,
  isLoaded: PropTypes.bool.isRequired,
  renderCustomButton: PropTypes.func,
};
MarketRoomViewList.defaultProps = {
  renderCustomButton: () => {},
};

export default MarketRoomViewList;
