import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import {
  Container,
  SwipeViews,
  SwipeView,
  Tab,
  Tabs,
  StaticContainer,
} from "./styles";

const swipeTabsPropTypes = {
  /** An array that render the tabs at the top of SwipeTabs */
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      errro: PropTypes.bool,
    })
  ).isRequired,

  /** Callback that is triggered when user swipe the children inside the SwipeTabs */
  onChange: PropTypes.func,

  variant: PropTypes.oneOf(["fullWidth", "scrollable", "standard", "fixed"]),

  /** Define the SwipeTabs position, default is "centered" */
  alignment: PropTypes.oneOf(["left", "centered"]),

  /** Define the SwipeTabs size, default is "regular" */
  size: PropTypes.oneOf(["small", "regular"]),

  /** If TRUE, remove the tabs , and just display the swipe views, default is "false" */
  disableTabs: PropTypes.bool,

  /** If TRUE, the position of swipe tabs need to be controlled by the "index" props, default is "false" */
  controlled: PropTypes.bool,

  /** This props is required only if "controlled" props is TRUE, this contorl the position of the swipe */
  index: PropTypes.number,

  /** Static element not affected by swipe, will be rendered in all tabs */
  staticChild: PropTypes.element,

  className: PropTypes.string,

  children: PropTypes.any.isRequired,

  childrenStyle: PropTypes.object,

  style: PropTypes.object,
};

const swipeTabsDefaultProps = {
  className: "swipe-tabs",
  onChange: () => {},
  variant: "standard",
  alignment: "centered",
  size: "regular",
  disableTabs: false,
  controlled: false,
  index: null,
  childrenStyle: null,
  staticChild: null,
  style: null,
};

/** SwipeTabs is an component to transform all his children into an SwipeableView.
 *
 * SwipeTabs use <a href="https://react-swipeable-views.com/">react-swipeable-view</a>.
 */
function SwipeTabs({
  className,
  tabs,
  children,
  alignment,
  onChange,
  variant,
  disableTabs,
  index,
  controlled,
  childrenStyle,
  staticChild,
  size,
  ...rest
}) {
  const [value, setValue] = React.useState(0);

  const indexControl = controlled ? index : value;

  const handleChange = (event, newValue) => {
    onChange(newValue);
    setValue(newValue);
  };

  return (
    <Container
      disableTabs={disableTabs}
      className={className}
      size={size}
      {...rest}
    >
      {!disableTabs && (
        <Tabs
          alignment={alignment}
          className="tabs"
          variant={variant}
          value={indexControl}
          onChange={handleChange}
        >
          {tabs.length > 0 &&
            tabs.map(tab => (
              <Tab
                key={tab.id}
                label={tab.name}
                icon={tab.icon}
                isErrored={tab.error}
                disabled={tab.hidden}
                style={{ display: tab.hidden && "none" }}
              />
            ))}
        </Tabs>
      )}
      {staticChild && <StaticContainer>{staticChild}</StaticContainer>}

      <SwipeViews
        disableTabs={disableTabs}
        className="swipeViews"
        index={indexControl}
        onChangeIndex={handleChange}
      >
        {React.Children.map(children, c => (
          <SwipeView className="swipeView" style={childrenStyle}>
            {c}
          </SwipeView>
        ))}
      </SwipeViews>
    </Container>
  );
}

SwipeTabs.propTypes = swipeTabsPropTypes;

SwipeTabs.defaultProps = swipeTabsDefaultProps;

export { SwipeTabs, swipeTabsPropTypes, swipeTabsDefaultProps };
