import React, { createContext } from "react";
import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";

// CUSTOM IMPORTS
import { Pip } from "~/shared/components/organisms/Pip";
import { pushState } from "~/shared/services/history";
import { openHome } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";

const PipContext = createContext(null);

const PipProvider = ({ children }) => {
  const location = useLocation();

  //* STATES
  const [isPipVisible, setIsPipVisible] = React.useState(false);
  const [pipData, setPipData] = React.useState({
    src: "",
    title: "",
    type: "",
    subTitle: "",
    returnPath: "",
    currentTime: 0,
    videoTabIndex: 0,
  });
  const [pipVideoData, setPipVideoData] = React.useState(null);

  const openPip = ({
    src,
    title,
    subTitle = null,
    type,
    currentTime,
    videoTabIndex,
  }) => {
    setIsPipVisible(true);
    setPipData({
      src,
      title,
      subTitle,
      type,
      returnPath: location.pathname,
      currentTime,
      videoTabIndex,
    });
    openHome();
  };

  const closePip = () => {
    setIsPipVisible(false);

    //* Clear pip
    setPipData(null);
  };

  const getPipVideoData = () => pipVideoData;

  const clearPipVideoData = () => setPipVideoData(null);

  return (
    <PipContext.Provider
      value={{
        openPip,
        closePip,
        getPipVideoData,
        clearPipVideoData,
        isPipVisible,
      }}
    >
      {children}

      {isPipVisible && isDataValid(pipData) && (
        <Pip
          pipData={pipData}
          onClose={closePip}
          onExpand={currentTime => {
            closePip();

            const returnUrl = `${pipData.returnPath}`;

            setPipVideoData({
              autoPlay: true,
              videoTabIndex: pipData.videoTabIndex,
              currentTime,
              type: pipData.type,
              src: pipData.src,
            });

            pushState(returnUrl);
          }}
        />
      )}
    </PipContext.Provider>
  );
};

PipProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function usePip() {
  const context = React.useContext(PipContext);

  if (!context) throw new Error("usePip must be used within PipProvider");

  return context;
}

export { PipProvider, usePip };
