import React from "react";
import PropTypes from "prop-types";
import { MdArrowBack } from "react-icons/md";
import { useSelector, useDispatch } from "react-redux";

// ACTIONS
import { signOut } from "~/modules/auth/store/auth/actions";
import {
  completeSignUp,
  updateProfile,
  updateAvatar,
} from "~/modules/signed/modules/client/store/userLogged/actions";

// CUSTOM IMPORTS
import { AvatarInput } from "~/shared/components/molecules";
import { Button, IconButton, buttonPropTypes } from "~/shared/components/atoms";
import DynamicFields from "~/modules/signed/modules/client/components/fragments/DynamicUserForm";

function UpdateProfileFragment({
  title,
  button,
  className,
  type,
  isCompleteSignUp,
}) {
  // REDUCER
  const dispatch = useDispatch();
  const { data: user, isLoading, isUpdatingAvatar } = useSelector(
    state => state.user.profile
  );

  function handleReturn() {
    dispatch(signOut());
  }

  function saveUser(data) {
    if (type === "completeSignUp") {
      dispatch(completeSignUp({ userData: data, userId: user.id }));
    } else {
      dispatch(updateProfile({ userData: data, userId: user.id }));
    }
  }

  const handleAvatarChange = photo => {
    const data = new FormData();

    data.append("avatar", photo);

    dispatch(updateAvatar(data, user.id));
  };

  return (
    <div className={className}>
      {isCompleteSignUp && (
        <IconButton
          className="auth-back-button"
          type="button"
          onClick={handleReturn}
        >
          <MdArrowBack size={16} />
        </IconButton>
      )}

      <div style={{ width: "100%" }}>
        <h3
          style={{
            fontSize: 36,
            fontWeight: "lighter",
            marginBottom: 24,
            textAlign: "center",
          }}
        >
          {title}
        </h3>

        <AvatarInput
          isLoading={isUpdatingAvatar}
          className="auth-profile-image"
          initialValue={user.avatar}
          onChange={photo => handleAvatarChange(photo)}
        />

        <DynamicFields
          noPasswordValidation={type === "updateProfile"}
          user={user}
          onSubmit={saveUser}
          footerComponent={
            <Button
              {...button}
              style={{ marginBottom: 40, width: "100%", marginTop: 24 }}
              className="auth-button"
              type="submit"
              loading={isLoading}
            />
          }
        />
      </div>
    </div>
  );
}

UpdateProfileFragment.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  type: PropTypes.string,
  button: PropTypes.shape(buttonPropTypes),
  isCompleteSignUp: PropTypes.bool,
};

UpdateProfileFragment.defaultProps = {
  title: "",
  className: null,
  type: "updateProfile",
  button: null,
  isCompleteSignUp: true,
};

export default UpdateProfileFragment;
