import api from "~/shared/services/api";

export const listLanguages = () => Promise.resolve(api.get(`/admin/language`));

export const showLanguage = ({ languageId }) =>
  Promise.resolve(api.get(`/admin/language/${languageId}`));

export const createLanguage = ({ data }) =>
  Promise.resolve(api.post(`/admin/language`, data));

export const updateLanguage = ({ data, languageId }) =>
  Promise.resolve(api.put(`/admin/language/${languageId}`, data));

export const deleteLanguage = ({ languageId }) =>
  Promise.resolve(api.delete(`/admin/language/${languageId}`));
