import React, { useState } from "react";
import PropTypes from "prop-types";
import { MdCameraAlt } from "react-icons/md";

//* CUSTOM IMPORTS
import { Spinner, Card } from "../../atoms";
import { Box, Avatar, TouchableWrapper } from "./styles";

const avatarInputPropTypes = {
  className: PropTypes.string,

  /** Initial value for preview */
  initialValue: PropTypes.string,

  /** Callback that triggered when user upload an file, return the file data */
  onChange: PropTypes.func,

  /** Change the preview for an <b>Spinner</b> */
  isLoading: PropTypes.bool,
};

const avatarInputDefaultProps = {
  initialValue: null,
  className: null,
  onChange: () => {},
  isLoading: false,
};

/** File input to add profile images */
function AvatarInput({ className, initialValue, onChange, isLoading }) {
  //* STATES
  const [photo, setPhoto] = useState(initialValue);

  //* FUNCTIONS
  const handleInputChange = e => {
    const photoValue = e.target.files[0];

    setPhoto(URL.createObjectURL(photoValue));

    onChange(photoValue);

    e.target.value = null;
  };

  return (
    <Box className={className}>
      {isLoading ? (
        <Card style={{ width: 120, height: 120, margin: "0 auto" }}>
          <Spinner centered />
        </Card>
      ) : (
        <TouchableWrapper>
          <label htmlFor="imageInput">
            <Avatar size={120} src={photo} />

            <div className="photo-label">
              <MdCameraAlt size={16} />
              Adicionar Foto
            </div>

            <input
              name="file"
              type="file"
              id="imageInput"
              accept="image/*"
              onChange={e => handleInputChange(e)}
            />
          </label>
        </TouchableWrapper>
      )}
    </Box>
  );
}

AvatarInput.propTypes = avatarInputPropTypes;

AvatarInput.defaultProps = avatarInputDefaultProps;

export { AvatarInput, avatarInputPropTypes, avatarInputDefaultProps };
