import api from "~/shared/services/api";

export const createCategory = ({ categoryData, type }) =>
  Promise.resolve(
    api.post(`/admin/category`, {
      ...categoryData,
      type,
    })
  );

export const updateCategory = ({ categoryData, type, categoryId }) =>
  Promise.resolve(
    api.put(`/admin/category/${categoryId}`, {
      ...categoryData,
      type,
    })
  );

export const deleteCategory = ({ categoryId }) =>
  Promise.resolve(api.delete(`/admin/category/${categoryId}`));

export const reorderCategory = ({ reorderedList }) =>
  Promise.resolve(
    api.put(`/admin/order/category`, {
      data: reorderedList,
    })
  );
