import { isDataValid } from "~/shared/utils/validations";

export function addItem({ newItem, list }) {
  const auxList = list;

  if (isDataValid(newItem)) {
    auxList.push(newItem);

    return auxList;
  }

  return list;
}

export function unshitItem({ newItem, list }) {
  const auxList = list;

  if (isDataValid(newItem)) {
    auxList.unshift(newItem);

    return auxList;
  }

  return list;
}

export function updateItem({ list, updatedItem }) {
  if (isDataValid(updatedItem)) {
    const auxList = list;

    const index = auxList.findIndex(obj => obj.id === updatedItem.id);

    auxList[index] = updatedItem;

    return auxList;
  }

  return list;
}

export function deleteItem({ list, itemId }) {
  const auxList = list;

  const filteredArray = auxList.filter(obj => obj.id !== itemId);

  return filteredArray;
}

export function handleSelectItem({
  itemId,
  list,
  setList,
  onSelect = () => {},
  onUnselect = () => {},
}) {
  // Find the index of id
  const alreadySelected = list.findIndex(item => item === itemId);

  // If do not have de itemId inside array will return - 1
  if (alreadySelected >= 0) {
    // Receive all the array less the itemId
    const filteredTags = list.filter(item => item !== itemId);

    setList(filteredTags);
    onUnselect(filteredTags);
  } else {
    setList([...list, itemId]);
    onSelect([...list, itemId]);
  }
}
