import styled, { keyframes } from "styled-components";

const animation = keyframes`
  from{
    transform: scaleX(1);

  }

  to{
    transform: scaleX(0);
  }
`;

export const Container = styled.div`
  position: relative;
`;

export const Progress = styled.div`
  position: absolute;
  width: 100%;
  height: 5px;
  background: ${props => props.theme.colors.accent} 0% 0% no-repeat padding-box;
  transform-origin: left;
  animation: linear ${animation} ${props => props.animationTime}s;
  animation-play-state: ${props => props.animationStatus};
`;
