import styled, { keyframes } from "styled-components";

//* CUSTOM IMPORTS
import { Card as CardC } from "../../atoms";

const showAnimation = keyframes`
  from{
    width: 94px;
  }

  to{
    width: 220px;
  }
`;

const hideAnimation = keyframes`
  from{
    width: 220px;
  }

  to{
    width: 94px;
  }
`;

export const Container = styled(CardC)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  animation: ${props => (props.showMoreEmojis ? showAnimation : hideAnimation)}
    0.3s ease-in-out;
`;

export const MoreEmojisButton = styled.button`
  width: 33px;
  height: 33px;
  min-width: 33px;
  min-height: 33px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${props => props.theme.colors.accent};
  border-radius: 50%;
  opacity: 0.56;
  transition: opacity 0.3s;

  svg {
    color: #fff;
    height: 24px;
    width: 24px;
  }

  &:hover {
    opacity: 1;
  }
`;

const emojiAnimation = keyframes`
  from{
    top: -50px;
    opacity: 1;
    max-height: 33px;
    max-width: 33px;
  }

  to{
    top: -300px;
    opacity: 0;
    max-height: 33px;
    max-width: 33px;
  }
`;

export const Emoji = styled.div`
  position: relative;
  display: ${props => (props.isVisible ? "flex" : "none")};

  #_009-like {
    path {
      fill: ${props => props.theme.colors.accent};
    }
  }

  .animated-emoji {
    position: absolute;
    left: 0;
    top: 0;

    animation: ${emojiAnimation} ${props => `${props.emojiAnimTime}ms`} ease-out;
  }

  button {
    position: relative;
    margin-right: 10px;
    height: 33px;
    width: 33px;
    background: none;
    border: none;
    transition: transform 0.3s;

    svg,
    img {
      width: 100%;
    }

    &:hover {
      transform: scale(1.25);
    }
  }
`;
