import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import {
  Button,
  IconButton,
  Icon,
} from "../../../../../../../shared/components/atoms";
import { useTranslation } from "../../../../../../../shared/hooks/translation";
import { stopPropagation } from "../../../../../../../shared/utils/events";

//* APIS
import { addBookmark, removeBookmark } from "../../../apis/bookmark";

const bookmarkButtonPropTypes = {
  className: PropTypes.string,

  /** Defines the variant of the component, default is icon */
  variant: PropTypes.oneOf(["button", "icon"]),

  /** if TRUE change the button color */
  isSelected: PropTypes.bool,

  /** Type of the content to be added/removed from bookmark (API param) */
  apiType: PropTypes.oneOf([
    "partners",
    "speakers",
    "posters",
    "rooms",
    "schedules",
  ]).isRequired,

  /** ref_id of the content to be added/removed from bookmark (param to the API) */
  apiId: PropTypes.number.isRequired,
};

const bookmarkButtonDefaultProps = {
  className: null,
  variant: "button",
  isSelected: false,
};

const BookmarkButton = ({
  className,
  variant,
  isSelected,
  apiType,
  apiId,
  ...rest
}) => {
  // STATES
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);
  const [stateSelected, setStateSelected] = React.useState(isSelected);

  const handleClick = async () => {
    setStateSelected(!stateSelected);
    if (stateSelected) {
      await removeBookmark({ type: apiType, ref_id: apiId });
    } else {
      await addBookmark({ type: apiType, ref_id: apiId });
    }
  };

  const handleColorIcon = () => {
    if (stateSelected) return colors.accent;

    return colors.smoked;
  };

  return variant === "button" ? (
    <Button
      variant={stateSelected ? "contained" : "outlined"}
      style={{ position: "relative" }}
      className={className}
      icon={<Icon iconName="heart" variant="white" />}
      tip={`${translate("common.bookMarkContent")}`}
      tipSize={200}
      onClick={e => stopPropagation(e, handleClick)}
      {...rest}
    />
  ) : (
    <IconButton
      isSelected={isSelected}
      onClick={e => stopPropagation(e, handleClick)}
      color={handleColorIcon}
    >
      <Icon iconName="heart" />
    </IconButton>
  );
};

BookmarkButton.propTypes = bookmarkButtonPropTypes;

BookmarkButton.defaultProps = bookmarkButtonDefaultProps;

export { BookmarkButton, bookmarkButtonPropTypes, bookmarkButtonDefaultProps };
