/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

//* APIs
import { socketApi } from "~/shared/services/api";

//* ACTIONS
import { deleteMessageSucessful, deleteMessageFailed } from "./actions";

//* CUSTOM IMPORTS
import { validResponseStatus } from "~/shared/utils/apis";
import { subscribe } from "~/shared/services/webSocket";
import types from "./types";

export function* sendMessage({ payload }) {
  try {
    const { message, broadcastRoomId, messageRepliedId } = payload;

    const channel = yield subscribe(`messageroom:${broadcastRoomId}`);

    channel.emit("sendMessage", {
      message,
      message_replied_id: messageRepliedId,
    });
  } catch (err) {
    console.log(err);
  }
}
export function* sendPinnedMessage({ payload }) {
  try {
    const { messageId, broadcastRoomId } = payload;

    const channel = yield subscribe(`messageroom:${broadcastRoomId}`);

    channel.emit("sendPinnedMessage", {
      message_id: messageId,
    });
  } catch (err) {
    console.log(err);
  }
}

export function* blockUserChat({ payload }) {
  try {
    const { broadcastRoomId, userId } = payload;

    const response = yield call(socketApi.post, "/admin/block", {
      user_id: userId,
      table_ref: broadcastRoomId,
      type: "chat_room",
    });

    if (validResponseStatus(response.status)) {
      yield put(deleteMessageSucessful());
    }
  } catch (err) {
    yield put(deleteMessageFailed());
  }
}

export function* deleteMessage({ payload }) {
  try {
    const { messageId } = payload;

    yield call(socketApi.delete, `/admin/message/${messageId}`);
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest(types.channel.SEND_MESSAGE, sendMessage),
  takeLatest(types.channel.admin.BLOCK_USER, blockUserChat),
  takeLatest(types.channel.admin.DELETE_MESSAGE, deleteMessage),
  takeLatest(types.channel.SEND_PINNED_MESSAGE, sendPinnedMessage),
]);
