import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Box } from "../../atoms";
import { Empty, emptyPropTypes } from "..";
import Loader from "../cellLoaders/SingleBarLoader";
import {
  TableRow,
  tableRowPropTypes,
  tableRowDefaultProps,
} from "./components/TableRow";
import { Container, RowsContainer } from "./styles";

const tablePropTypes = {
  /** Render the titles of the Table based on this array */
  titles: PropTypes.arrayOf(PropTypes.string),

  /** An Array with objects, each attribute of this object will become an TableData */
  rows: PropTypes.arrayOf(PropTypes.object).isRequired,

  /** Render the TableLoader */
  isLoading: PropTypes.bool,

  /** If has data, render an <b>Empty</b> component, see all props in <b>Molecules/Empty</b> */
  empty: PropTypes.shape(emptyPropTypes),

  size: tableRowPropTypes.size,

  /** Define the <b>RowContainer</b> styles variant, default is "empty" */
  containerVariant: PropTypes.oneOf(["empty, contained"]),

  /** Control the number of <b>TableLoaderCell</b> that will be rendered, deafult is 3 */
  loaderListSize: PropTypes.number,

  variant: tableRowPropTypes.variant,

  /** Arary of width in percentage for each colum.
   *
   * Example: ['30%', '70%]
   */
  columnSizes: PropTypes.arrayOf(PropTypes.string),
};

const tableDefaultProps = {
  titles: [],
  isLoading: false,
  empty: null,
  variant: tableRowDefaultProps.variant,
  size: tableRowDefaultProps.size,
  containerVariant: "empty",
  loaderListSize: 3,
  columnSizes: [],
};

const TableLoaderCell = () => (
  <Box display="flex" justifyContent="space-around" marginBottom="32px">
    <Loader />
    <Loader />
  </Box>
);

/** A siple Table to display data. */
function Table({
  titles,
  rows,
  containerVariant,
  variant,
  size,
  loaderListSize,
  isLoading,
  empty,
  columnSizes,
  ...rest
}) {
  //* FUNCTIONS
  function setLoaders() {
    const loaderArray = [];

    for (let i = 0; i < loaderListSize; i += 1) {
      loaderArray.push(i);
    }

    return loaderArray;
  }

  return (
    <Container id="table" containerVariant={containerVariant} {...rest}>
      {isLoading ? (
        <RowsContainer id="rowsContainer" style={{ padding: "20px 20px 8px" }}>
          <Box width="100%">
            {setLoaders().map(() => (
              <TableLoaderCell />
            ))}
          </Box>
        </RowsContainer>
      ) : (
        <>
          {rows.length < 1 ? (
            empty && <Empty {...empty} />
          ) : (
            <>
              <TableRow
                id="tableTitles"
                size="small"
                variant="title"
                data={titles.map(title => ({ value: title }))}
                columnSizes={columnSizes}
              />

              <RowsContainer id="rowsContainer">
                {rows.map((row, index) => (
                  <TableRow
                    size={size}
                    variant={variant}
                    isTheLastOne={index === rows.length - 1}
                    data={Object.values(row).map(value => ({ value }))}
                    columnSizes={columnSizes}
                  />
                ))}
              </RowsContainer>
            </>
          )}
        </>
      )}
    </Container>
  );
}

Table.propTypes = tablePropTypes;

Table.defaultProps = tableDefaultProps;

export {
  Table,
  tablePropTypes,
  tableDefaultProps,
  TableRow,
  tableRowPropTypes,
  tableRowDefaultProps,
};
