import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { ProfileConnectionCell } from "./styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { CONNECTION_TYPES } from "~/shared/utils/types";
import CheckListCellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import DisconnectModal from "~/modules/signed/modules/client/components/modals/DisconnectModal";
import { SearchBar } from "../../styles";

function CommonConnectionTab({ callConnections }) {
  // STATES
  const {
    commonCurrentPage,
    commonConnections,
    updateConnection,
    paginateConnections,
    profileNavigation,
    isUserLoaded,
    isSearching,
    listUserConnections,
    setCommonConnections,
    searchString,
    setSearchString,
  } = useProfile();
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();
  const [showDisconnectModal, setShowDisconnectModal] = React.useState(false);
  const [clickedUser, setClickedUser] = React.useState({});

  // Fill other user common connections to my user array
  React.useEffect(() => {
    if (!commonConnections && callConnections)
      listUserConnections(
        commonConnections.page,
        CONNECTION_TYPES.COMMON,
        setCommonConnections
      );
  }, [
    callConnections,
    commonConnections,
    commonConnections.page,
    listUserConnections,
    setCommonConnections,
  ]);

  const fillItems = connected => {
    const items = [
      {
        id: 1,
        name: translate("entities.profile.header.disconnect"),
        function: () => {
          setClickedUser(connected);
          setShowDisconnectModal(true);
        },
        color: colors.redWrong,
      },
    ];

    return items;
  };

  function renderMenu(common) {
    return {
      buttonIcon: <Icon iconName="menu" />,
      fillItems: () => fillItems(common),
    };
  }

  // FUNCTIONS
  const renderCell = common => (
    <ProfileConnectionCell
      infos={{ ...common, title: common.name }}
      avatar={{ src: common.avatar, size: 68 }}
      simpleMenu={renderMenu(common)}
      onClick={() => profileNavigation(common)}
    />
  );

  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate(
            "entities.profile.body.connection.emptyCommonMessage"
          )}
          description={translate(
            "entities.profile.body.connection.emptyCommonDescription"
          )}
          icon={<Icon iconName="friends" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="friends" />}
      />
    );
  };

  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);
          listUserConnections(
            commonConnections.page,
            CONNECTION_TYPES.COMMON,
            setCommonConnections,
            value
          );
        }}
      />
      <CellList
        isLoaded={isUserLoaded && !commonConnections.isPaging && !isSearching}
        renderCellLoader={() => (
          <CheckListCellLoader
            avatarSize={68}
            infosX={90}
            height={68}
            style={{ marginRight: "160px", marginBottom: "32px" }}
          />
        )}
        emptyComponent={handleEmptyMessage()}
        data={commonConnections.data}
        renderCell={common => renderCell(common)}
        loaderListSize={9}
        gap={16}
      />

      <Pagination
        currentPage={commonCurrentPage}
        handlePagination={page =>
          paginateConnections(CONNECTION_TYPES.CONNECTED, page)
        }
        lastPage={commonConnections.lastPage}
      />
      <DisconnectModal
        user={clickedUser}
        options={null}
        show={showDisconnectModal}
        onCancel={() => setShowDisconnectModal(false)}
        onClose={() => setShowDisconnectModal(false)}
        onClick={() => {
          setShowDisconnectModal(true);
          updateConnection(
            clickedUser.id,
            CONNECTION_TYPES.DISCONNECTED,
            CONNECTION_TYPES.COMMON
          );
          setShowDisconnectModal(false);
        }}
      />
    </div>
  );
}

CommonConnectionTab.propTypes = {
  callConnections: PropTypes.bool.isRequired,
};

CommonConnectionTab.defaultProps = {};

export default CommonConnectionTab;
