import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import {
  createSpeaker,
  updateSpeaker,
} from "~/modules/signed/modules/cms/modules/admin/store/speakers/actions";
import { isDataValid } from "~/shared/utils/validations";
import history from "~/shared/services/history";
import {
  Modal,
  Form,
  FileInput,
  Input,
  TranslateInput,
  Footer,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function SpeakerModal({ speaker }) {
  const dispatch = useDispatch();
  const updateStatus = useSelector(
    state => state.adminSpeakers.list.updateStatus
  );

  //* STATES
  const [show, setShow] = React.useState(true);
  const [formData, setFormData] = React.useState({});

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (isDataValid(speaker)) setFormData(speaker);
  }, [speaker]);

  const handleOnClose = () => {
    setShow(false);

    setTimeout(() => history.goBack(), 300);
  };

  React.useMemo(() => {
    if (updateStatus === "FINISH") handleOnClose();
  }, [updateStatus]);

  const handleSUbmit = data => {
    const { bio, name, company, role, picture } = data;

    const form = {
      name,
      company,
      role,
      bio,
      picture,
    };

    if (isDataValid(speaker)) {
      dispatch(updateSpeaker(speaker.id, form));
    } else {
      dispatch(createSpeaker(form));
    }
  };

  return (
    <Modal
      title={
        speaker
          ? translate("entities.speakers.edit")
          : translate("entities.speakers.add")
      }
      show={show}
      onClose={handleOnClose}
    >
      <Form
        onSubmit={data => handleSUbmit(data)}
        yupValidation={{
          name: Yup.string().required(translate("handlers.name.isRequired")),
          company: Yup.string(),
          bio: Yup.string(),
        }}
        initialData={formData}
      >
        <FileInput
          name="picture"
          title={translate("entities.speakers.photo")}
          initialValue={isDataValid(speaker) ? speaker.picture : null}
          heigth={300}
          width={300}
        />

        <div className="create-speaker-inputs">
          <Input
            name="name"
            title={translate("components.input.speakerName.title")}
            placeholder={translate("components.input.speakerName.placeholder")}
          />

          <div className="create-speaker-company-role">
            <TranslateInput
              className="create-speaker-company"
              name="company"
              title={translate("components.input.speakerCompany.title")}
              placeholder={translate(
                "components.input.speakerCompany.placeholder"
              )}
            />
            <TranslateInput
              className="create-speaker-role"
              name="role"
              title={translate("components.input.speakerRole.title")}
              placeholder={translate(
                "components.input.speakerRole.placeholder"
              )}
            />
          </div>

          <TranslateInput
            name="bio"
            isTextArea
            title={translate("components.input.speakerBio.title")}
            placeholder={translate("components.input.speakerBio.placeholder")}
          />

          <Footer
            onCancel={handleOnClose}
            isLoading={updateStatus === "PENDING"}
          />
        </div>
      </Form>
    </Modal>
  );
}

SpeakerModal.propTypes = {
  speaker: PropTypes.object,
};

SpeakerModal.defaultProps = {
  speaker: null,
};

export default SpeakerModal;
