import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import MaterialSwitch from "@material-ui/core/Switch";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Container, Title } from "./styles";

const switchPropTypes = {
  className: PropTypes.string,

  /** Add an title above the Switch */
  title: PropTypes.string,

  /** If TRUE, turn in Switch */
  isChecked: PropTypes.bool.isRequired,

  /** Callback that is triggered when user click in Switch */
  onChange: PropTypes.func,
};

const switchDefaultProps = {
  title: null,
  className: null,
  onChange: () => {},
};

/** Switch component extends the Material Ui Switch
 *
 * see more here: <b>https://material-ui.com/api/switch/</b>
 */
function Switch({ className, title, isChecked, onChange }) {
  const { colors } = React.useContext(ThemeContext);

  const CustomSwitch = withStyles({
    switchBase: {
      color: colors.white,
      "&$checked": {
        color: colors.accent,
      },
      "&$checked + $track": {
        backgroundColor: colors.accent,
      },
    },
    checked: {},
    track: {},
  })(MaterialSwitch);

  return (
    <Container className={className}>
      {title && <Title>{title}</Title>}

      <CustomSwitch
        checked={isChecked}
        onChange={onChange}
        name="checkedA"
        inputProps={{ "aria-label": "secondary checkbox" }}
      />
    </Container>
  );
}

Switch.propTypes = switchPropTypes;

Switch.defaultProps = switchDefaultProps;

export { Switch, switchPropTypes, switchDefaultProps };
