import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Line as LineC } from "../../atoms";
import { Modal as ModalC } from "../Modal";
import { FooterButtons as FooterButtonsC } from "../FooterButtons";

export const Modal = styled(ModalC)`
  padding: 0;

  ${props => {
    switch (props.variant) {
      case "error":
        return css`
          .modal-title {
            color: ${props.theme.colors.redWrong};
          }

          .dialog-modal-description {
            strong {
              color: ${props.theme.colors.redWrong};
            }
          }
        `;

      default:
        return css`
          .modal-title {
            color: ${props.theme.colors.accent};
          }

          .dialog-modal-description {
            strong {
              color: ${props.theme.colors.accent};
            }
          }
        `;
    }
  }}
`;

export const Content = styled.div``;

export const Line = styled(LineC)``;

export const Description = styled.p`
  font-size: 14px;
  margin-top: 16px;
  color: ${props => props.theme.colors.normal};

  strong {
    color: ${props => props.theme.colors.accent};
    text-transform: uppercase;
    margin: 0 4px;
  }
`;

export const FooterButtons = styled(FooterButtonsC)`
  margin-top: 24px;
`;
