import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { ScrollContainer, LeftContainer, TextData, TextLabel } from "./styles";
import { getFullHours, getFullDate } from "~/shared/utils/dates";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function EmailInformation({ email }) {
  // STATES
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  return (
    <ScrollContainer>
      <LeftContainer>
        <TextLabel>
          {translate("entities.emails.emailDetails.name").toUpperCase()}
        </TextLabel>
        <TextData>{dTranslate(email.name)}</TextData>
        <TextLabel>
          {translate("entities.emails.emailDetails.about").toUpperCase()}
        </TextLabel>
        <TextData>{dTranslate(email.about)}</TextData>
        <TextLabel>
          {translate("entities.emails.emailDetails.description").toUpperCase()}
        </TextLabel>
        <TextData>{dTranslate(email.description)}</TextData>
        <TextLabel>
          {translate("entities.emails.emailDetails.sendedAt").toUpperCase()}
        </TextLabel>
        <TextData>{`${getFullDate(email.sended_at)} - ${getFullHours(
          email.sended_at
        )}`}</TextData>
        <TextLabel>
          {translate("entities.emails.emailDetails.senderEmail").toUpperCase()}
        </TextLabel>
        <TextData>{email.sender_email}</TextData>
        <TextLabel>
          {translate("entities.emails.emailDetails.senderName").toUpperCase()}
        </TextLabel>
        <TextData>{email.sender_name}</TextData>
      </LeftContainer>
    </ScrollContainer>
  );
}

EmailInformation.propTypes = {
  email: PropTypes.object.isRequired,
};

EmailInformation.defaultProps = {};

export default EmailInformation;
