import styled from "styled-components";

//* CUSTOM IMPORTS
import { Button, Card as CardC, Typography as TypographyC } from "../../atoms";

export const Card = styled(CardC)`
  position: relative;
`;

export const CardDot = styled.div`
  border-radius: 50%;
  height: 8px;
  width: 8px;
  background-color: ${props => props.color};

  position: absolute;
  top: 0;
  left: 0;
  margin-top: 16px;
  margin-left: 16px;
`;

export const AccessReports = styled.ul`
  display: block;
  width: 100%;
  text-align: center;
  margin: 0;

  li {
    font-size: 16px;
    padding: 6px 0;
    margin-bottom: 4px;
    border-bottom: 1px solid ${props => props.theme.colors.shadow};
    display: flex;
    justify-content: space-between;
    width: 100%;
    text-transform: none;
  }
`;

export const AccessReportTitle = styled(TypographyC)`
  flex: 0.7;
  text-align: left;
  font-family: "Lato", sans-serif;
`;

export const AccessReportCounter = styled(TypographyC)`
  flex: 0.3;
  text-align: right;
  color: ${props => props.theme.colors.strong};
`;

export const LinkButton = styled(Button)`
  width: 100%;
  margin-top: 10px;
`;

export const Typography = styled(TypographyC)``;
