import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listExtraButtons,
  clearExtraButtons,
  deleteExtraButton,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

//* CUSTOM IMPORTS
import Loader from "~/shared/components/molecules/cellLoaders/SingleBarLoader";
import { openExtraButtonModal } from "~/shared/routes/navigation";
import { IconedButton, Box, Typography } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import { ExtraButtonCell } from "~/shared/components/organisms/cells/ExtraButtonCell";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const extraButtonListPropTypes = {
  title: PropTypes.string.isRequired,

  buttonLabel: PropTypes.string.isRequired,

  /** The room ID the extra button belongs to */
  refId: PropTypes.number.isRequired,

  /** The type of button that will be created, you can acess all the REF_TYPES
   * in <b>modules/signed/modules/admin/store/extraLinks/action</b>.
   */
  refType: PropTypes.string.isRequired,
};

const ExtraButtonList = ({ title, buttonLabel, refId, refType, ...rest }) => {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: extraButtons, isLoaded, isUpdating, isDeleting } = useSelector(
    state => state.adminExtraLinks.extraButtons
  );

  //* STATES
  const [buttonId, setButtonId] = React.useState(null);

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(refId))
      dispatch(listExtraButtons(refId, refType));

    return () => {
      dispatch(clearExtraButtons());
      isMounted = false;
    };
  }, [dispatch, refId, refType]);

  const renderCell = extraButton => {
    return (
      <ExtraButtonCell
        iconName={extraButton.type}
        entity={translate("common.button")}
        entityName={dTranslate(extraButton.label)}
        onClick={() => {
          setButtonId(extraButton.id);
          openExtraButtonModal(refType, refId, extraButton);
        }}
        onEdit={() => {
          setButtonId(extraButton.id);
          openExtraButtonModal(refType, refId, extraButton);
        }}
        onDelete={() => {
          setButtonId(extraButton.id);
          dispatch(deleteExtraButton(extraButton.id));
        }}
      >
        {dTranslate(extraButton.label)}
      </ExtraButtonCell>
    );
  };

  return (
    <Box {...rest}>
      <Typography
        className="normal small uppercase bold"
        style={{ marginBottom: 10 }}
      >
        {title}
      </Typography>

      <IconedButton
        type="button"
        onClick={() => openExtraButtonModal(refType, refId)}
      >
        {buttonLabel}
      </IconedButton>

      <CellList
        cellId={buttonId}
        isUpdating={isUpdating === "PENDING"}
        isDeleting={isDeleting === "PENDING"}
        isLoaded={!refId ? true : isLoaded}
        data={extraButtons}
        renderCell={renderCell}
        gap={10}
        orientation="column"
        renderCellLoader={() => <Loader height={40} width={300} />}
        containerStyle={{ marginTop: 20 }}
        emptyComponent={
          <Empty
            message={translate("entities.extraButtons.empty.message")}
            description={translate("entities.extraButtons.empty.description")}
            style={{ alignItems: "flex-start" }}
          />
        }
      />
    </Box>
  );
};

ExtraButtonList.propTypes = extraButtonListPropTypes;

export { ExtraButtonList };
