import styled, { css } from "styled-components";
import { shade } from "polished";
import { MenuItem as MUIMenuItem, Menu as MUIMenu } from "@material-ui/core";

export const Container = styled.div``;

export const Menu = styled(MUIMenu)`
  .MuiPaper-root {
    box-shadow: 0 5px 7px ${props => props.theme.colors.shadow};
  }
`;

export const MenuItem = styled(MUIMenuItem)`
  ${props =>
    props.withLine &&
    css`
      p {
        border-top: 1px solid ${props.theme.colors.smoked};
        padding-top: 8px;
      }
    `}

  &:hover {
    p,
    svg {
      color: ${props =>
        props.color ? shade(0.15, props.color) : props.theme.colors.accent};
    }
  }
`;

export const MenuItemName = styled.p`
  margin: 0;
  width: 100%;
  text-align: right;
  font-size: 12px;
  font-weight: bold;
  text-transform: ${props => (props.uppercaseItems ? "uppercase" : "normal")};
  color: ${props => props.color || props.theme.colors.opaque};
`;

export const MenuItemIcon = styled.div`
  margin-right: 8px;
  position: relative;
  bottom: 2px;

  svg {
    height: 16px;
    width: 16px;
    color: ${props => (props.color ? props.color : props.theme.colors.opaque)};
  }
`;
