/* eslint-disable import/no-cycle */
/* eslint-disable no-console */
import { toast } from "react-toastify";
import history from "../../services/history";
import { isDataValid } from "../validations";
import i18n from "../../translation/i18n";

// CUSTOM IMPORTS
import { store } from "../../store";
import { signOut } from "../../../modules/auth/store/auth/actions";

function ResponseException(message) {
  this.message = message;
  this.name = "ResponseException";
}

export const validResponseStatus = status => {
  if (Number(status) >= 200 && Number(status) <= 300) return true;

  throw new ResponseException("Invalid status response");
};

export const showAPIErrors = async err => {
  // Sign out the user case has an unauthorized request
  if (isDataValid(err.response)) {
    if (isDataValid(err.response.status))
      if (err.response.status === 401) store.dispatch(signOut());

    if (isDataValid(err.response.data)) {
      const { error, errors, name } = err.response.data;

      if (isDataValid(name)) {
        if (name === "access_revoked")
          history.push("/login", {
            alert: {
              title: i18n.t("common.accessDuplicated"),
              description: i18n.t("common.accessDuplicatedMessage"),
            },
          });

        if (name === "unauthorized")
          history.push("/login", {
            alert: {
              title: i18n.t("entities.users.unauthorized"),
              description: error || i18n.t("entities.users.invalid"),
            },
          });
      } else {
        // Case errors came as a Array display all errors inside it.
        if (isDataValid(errors)) {
          if (errors.length > 0) errors.map(e => toast.error(e));
        }

        // If error is only a message
        if (isDataValid(error)) toast.error(error);
      }
    }
  }
};

export const showAPIMessages = msg => {
  if (msg !== undefined && msg !== null) toast.info(msg);
};

export const API_STATUS = {
  IDLE: 1,
  PENDING: 2,
  PAGING: 3,
  CREATING: 4,
  UPDATING: 5,
  DELETING: 6,
  SEARCHING: 7,
  FAILED: 8,
  SUCCESS: 9,
};
