import React from "react";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import { setPartnerIds } from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";

// CUSTOM IMPORTS
import { ScrollContainer, PartnerCheckList } from "./styles";

function BroadcastRoomSponsorship() {
  // REDUCER
  const dispatch = useDispatch();
  const broadcastRoomReducer = useSelector(
    state => state.adminBroadcastRooms.object
  );
  const { partnerIds } = broadcastRoomReducer && broadcastRoomReducer;

  return (
    <ScrollContainer>
      <PartnerCheckList
        selectedPartners={partnerIds}
        setSelectedPartners={partners => dispatch(setPartnerIds(partners))}
      />
    </ScrollContainer>
  );
}

export default BroadcastRoomSponsorship;
