import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { DefaultNotification } from "./components/Default";
import { ConnectionNotification } from "./components/Connection";
import { PointsNotification } from "./components/Gamification";
import { isTheSameDomain } from "~/shared/utils/strings";
import { useNotificationSocket } from "../webSocket/channels/notification";
import { pushState } from "~/shared/services/history";
import { useTranslation } from "~/shared/hooks/translation";
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { ProfileProvider } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useGamification } from "../gamification";

const NotificationContext = createContext({});

const AUTO_CLOSE_INITIAL_VALUE = 10000;

const NotificationProvider = ({ children }) => {
  const notificationRef = React.useRef(null);

  //* STATES
  const [notification, setNotification] = React.useState(null);
  const [queue, setQueue] = React.useState([]);
  const {
    newNotification: socketNotification,
    emitNotificationOpened,
  } = useNotificationSocket();
  const { updateMyRanking } = useGamification();

  notificationRef.current = notification;

  //* FUNCTIONS
  const { translate } = useTranslation();

  /** This function will display the newNotification in screen, if the screen
   * already has an notification add the newNotification into an queue.
   */

  /**
   * Example of newNotification object:
   *{
      created_at: "2021-06-29T14:02:47.000Z"
      description: "Descrição da notificação"
      id: 2
      link: null
      notification_id: 1
      title: "Nova notificação"
    }
   */
  const showNotification = React.useCallback(newNotification => {
    if (newNotification) {
      const autoClose = AUTO_CLOSE_INITIAL_VALUE;

      if (!notificationRef.current) {
        setNotification({ ...newNotification, autoClose });
      } else {
        setQueue(list => {
          list.push({ ...newNotification, autoClose });
          return list;
        });
      }
    }
  }, []);

  /** This Function monitores the notification socket, every time an new notification
   * comes, display it in the screen, only if has no notification be displaying.
   */
  React.useMemo(() => {
    if (socketNotification) {
      const { type } = socketNotification;

      switch (type) {
        /** Fot gamification notification, only show the notificaion
         * if inside the payload server sends the showable as TRUE.
         */
        case NOTIFICATION_TYPES.GAMIFICATION: {
          const { payload } = socketNotification;

          if (payload)
            if (payload.showable) showNotification(socketNotification);

          updateMyRanking(socketNotification.payload);

          break;
        }

        default:
          showNotification(socketNotification);
          break;
      }
    }
  }, [showNotification, socketNotification, updateMyRanking]);

  const handleAutoClose = React.useCallback(showTime => {
    setNotification(null);

    setQueue(queueState => {
      if (queueState.length > 0) {
        setNotification({ ...queueState[0], autoClose: showTime });
        queueState.shift();
      }

      return queueState;
    });
  }, []);

  const handleOpenNotification = (notificationId, link) => {
    if (isTheSameDomain(link)) {
      const domain = new URL(link);

      pushState(domain.pathname);
    } else {
      window.open(link);
    }

    emitNotificationOpened(notificationId);
  };

  const hasLinkVerify = () => {
    if (notification.link) {
      return true;
    }
    return false;
  };

  const renderNotification = () => {
    if (!notification) return null;

    switch (notification.type) {
      case NOTIFICATION_TYPES.CONNECTION:
        return (
          <ConnectionNotification
            notification={notification}
            onAutoClose={() => handleAutoClose(AUTO_CLOSE_INITIAL_VALUE)}
            unreadLink={translate("common.clickToAccess")}
            hasLink={false}
            link={null}
          />
        );

      case NOTIFICATION_TYPES.GAMIFICATION:
        return (
          <PointsNotification
            notification={notification}
            onAutoClose={() => handleAutoClose(AUTO_CLOSE_INITIAL_VALUE)}
          />
        );

      default:
        return (
          <DefaultNotification
            notification={notification}
            time={translate("common.now")}
            onAutoClose={() => handleAutoClose(AUTO_CLOSE_INITIAL_VALUE)}
            onLinkClick={() =>
              handleOpenNotification(notification.id, notification.link)
            }
            unreadLink={translate("common.clickToAccess")}
            hasLink={hasLinkVerify()}
          />
        );
    }
  };

  return (
    <NotificationContext.Provider
      value={{ queue, showNotification, notification }}
    >
      <ProfileProvider>
        {children}

        {renderNotification()}
      </ProfileProvider>
    </NotificationContext.Provider>
  );
};

NotificationProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

const useNotification = () => {
  const context = useContext(NotificationContext);

  if (!context)
    throw new Error("useNotification must be used within NotificationProvider");

  return context;
};

export { NotificationProvider, useNotification };
