import styled, { css } from "styled-components";
import { lighten } from "polished";

const calculateLeftDeg = progress => {
  let deg;

  if (progress >= 50) {
    deg = 180;
  } else {
    deg = progress * 3.6;
  }

  return `${deg}deg`;
};

const calculateRighttDeg = progress => {
  let deg;

  if (progress >= 100) {
    deg = 180;
  } else {
    deg = (progress - 50) * 3.6;
  }

  return `${deg}deg`;
};
export const Progress = styled.span`
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  min-width: ${props => props.size}px;
  min-height: ${props => props.size}px;

  .circle .left .progress {
    transform: ${props => `rotate(${calculateLeftDeg(props.progress)})`};
  }

  .circle .left .progress {
    transition: transform 0.8s linear;
  }

  .circle .right .progress {
    transition: transform 0.8s linear;
    transition-delay: 0.8s;
  }

  .circle .right .progress {
    ${props =>
      props.progress > 50 &&
      css`
        transform: ${`rotate(${calculateRighttDeg(props.progress)})`};
      `}
  }

  position: relative;

  .circular .inner,
  .circular .circular .circle {
    position: absolute;
    z-index: 6;
    height: 100%;
    width: 100%;
    border-radius: 100%;
  }

  .circular .inner {
    top: 50%;
    left: 50%;
    height: ${props => props.size * 0.9}px;
    width: ${props => props.size * 0.9}px;
    margin: ${props => -(props.size * 0.9) / 2}px 0 0
      ${props => -(props.size * 0.9) / 2}px;
    background-color: ${props => lighten(0.33, props.theme.colors.accent)};
    border-radius: 100%;
  }

  .circular .circle {
    z-index: 1;
  }

  .circular .numb {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    font-size: 18px;
    font-weight: 500;
    color: ${props => props.theme.colors.accent};
  }

  .circular .bar {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: ${props => lighten(0.33, props.theme.colors.accent)};
    border-radius: 100%;
    clip: rect(
      0px,
      ${props => props.size}px,
      ${props => props.size}px,
      ${props => props.size / 2}px
    );
  }

  .circle .bar .progress {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 100%;
    clip: rect(
      0px,
      ${props => props.size / 2}px,
      ${props => props.size}px,
      0px
    );
  }
  .circle .bar .progress {
    background: ${props => props.theme.colors.accent};
  }

  .circle .right {
    z-index: 1;
    transform: rotate(180deg);
  }
`;

export const Percent = styled.p`
  margin: 0;
  font-size: ${props => props.size * 0.3}px;
  color: ${props => props.theme.colors.accent};
`;
