import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import { clearFormData } from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";

// APIS
import {
  createBroadcastRoom,
  updateBroadcastRoom,
} from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";

// CUSTOM IMPORTS
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { SwipeTabs } from "~/shared/components/molecules";
import { openAdminBroadcastRooms } from "~/shared/routes/navigation";
import { isDataValid, isListValid } from "~/shared/utils/validations";
import { getFullHours, getFullDate } from "~/shared/utils/dates";
import { Footer } from "./styles";

// TAB FRAGMENTS
import InformationTab from "./components/InformationTab";
import ScheduleTab from "./components/ScheduleTab";
import SponsorshipTab from "./components/SponsorshipTab";
import AccessibilityTab from "./components/AccessibilityTab";
import ButtonsTab from "./components/ButtonsTab";
import VisibilityTab from "./components/VisibilityTab";

import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function BroadcastRoomForm({ broadcastRoom, categoryId }) {
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  // REDUCER
  const dispatch = useDispatch();
  const { tagIds, partnerIds, scheduleIds, groupIds } = useSelector(
    state => state.adminBroadcastRooms.object
  );

  // EXTRA LINKS
  const { data: extraButtons } = useSelector(
    state => state.adminExtraLinks.extraButtons
  );
  const { data: extraPlayers } = useSelector(
    state => state.adminExtraLinks.extraPlayers
  );
  const { data: extraEmbeds } = useSelector(
    state => state.adminExtraLinks.extraEmbeds
  );

  // STATES
  const editMode = isDataValid(broadcastRoom);
  const [formData, setFormData] = React.useState({});
  const [formInitialData, setFormInitialData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);
  const [viewType, setViewType] = React.useState("public");
  const [hasQuestion, setHasQuestion] = React.useState(true);
  const [hasChat, setHasChat] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);
  const [showModal, setShowModal] = React.useState(false);

  // FUNCTIONS
  React.useEffect(() => {
    return () => {
      dispatch(clearFormData());
    };
  }, [dispatch]);

  React.useEffect(() => {
    if (isDataValid(broadcastRoom)) {
      const {
        video_player: videoPlayer,
        open_live: openLive,
        end_live: endLive,
        has_chat,
        has_question,
      } = broadcastRoom;
      const { title, description, view_type } = broadcastRoom.room;

      setViewType(view_type);
      setHasChat(has_chat);
      setHasQuestion(has_question);

      setFormInitialData({
        title,
        description,
        video_player: videoPlayer,
        initial_date: getFullDate(openLive),
        start_time: getFullHours(openLive),
        end_time: getFullHours(endLive),
      });
    }
  }, [broadcastRoom]);

  async function dispatchSave() {
    let extraButtonsIds = [];
    let extraPlayersIds = [];
    let embedIds = [];

    // Fill extraButtons Ids
    if (isListValid(extraButtons)) {
      extraButtonsIds = extraButtons.map(extraButton => extraButton.id);
    }

    // Fill extraPlayers Ids
    if (isListValid(extraPlayers)) {
      extraPlayersIds = extraPlayers.map(extraPlayer => extraPlayer.id);
    }

    // Fill embeds Ids
    if (isListValid(extraEmbeds)) {
      embedIds = extraEmbeds.map(extraEmbed => extraEmbed.id);
    }

    // Get all Form data input values
    const broadcastRoomData = {
      ...formData,
      category_id: categoryId,
      tagIds,
      partnerIds,
      scheduleIds,
      extraButtonsIds,
      extraPlayersIds,
      groupIds,
      view_type: viewType,
      has_chat: hasChat,
      has_question: hasQuestion,
      embedIds,
    };

    setIsLoading(true);

    if (editMode) {
      await updateBroadcastRoom({
        broadcastRoomData,
        broadcastRoomId: broadcastRoom.id,
      })
        .then(() => {
          openAdminBroadcastRooms();
          setIsLoading(false);
        })
        .catch(() => {
          setIsLoading(false);
        });
    } else {
      await createBroadcastRoom({ broadcastRoomData })
        .then(() => {
          openAdminBroadcastRooms();
        })
        .catch(() => setIsLoading(false));
    }
  }

  // TABS
  const tabs = [
    {
      id: 1,
      name: translate("entities.tabs.broadcastTabsInformation"),
      error: infoTabError,
    },
    { id: 2, name: translate("entities.tabs.broadcastTabsSchedule") },
    { id: 3, name: translate("entities.tabs.broadcastTabsSponsorship") },
    { id: 4, name: translate("entities.tabs.broadcastTabsExtrasBroadcast") },
    { id: 5, name: translate("entities.tabs.broadcastTabseExtraContents") },
    { id: 6, name: translate("entities.tabs.broadcastTabsRoomVisibility") },
  ];

  return (
    <>
      <SwipeTabs tabs={tabs} alignment="left" size="small">
        <Form
          id="broadcast-room-form"
          onSubmit={data => {
            setFormData(data);
            setShowModal(true);
          }}
          onError={() => setInfoTabError(true)}
          yupValidation={{
            title: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationTitle")
            ),
            description: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationDescription")
            ),
            initial_date: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationDate")
            ),
            start_time: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationStartTime")
            ),
            end_time: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationEndTime")
            ),
            video_player: Yup.string().required(
              translate("entities.broadcastRooms.yupValidationVideoLink")
            ),
          }}
          initialData={formInitialData}
        >
          <InformationTab
            broadcastRoom={broadcastRoom}
            chatControls={{ hasChat, setHasChat }}
            questionControls={{ hasQuestion, setHasQuestion }}
          />
        </Form>

        <ScheduleTab broadcastRoom={broadcastRoom} />

        <SponsorshipTab />

        <AccessibilityTab broadcastRoom={broadcastRoom} />

        <ButtonsTab broadcastRoom={broadcastRoom} />

        <VisibilityTab setViewType={setViewType} viewType={viewType} />
      </SwipeTabs>

      <Footer
        type="submit"
        formId="broadcast-room-form"
        isLoading={isLoading}
        showModal={showModal}
        onCancel={openAdminBroadcastRooms}
        entity={translate("entities.broadcastRooms.singular")}
        entityName={dTranslate(formData.title)}
        modalType={editMode ? "update" : "create"}
        onConfirm={dispatchSave}
      />
    </>
  );
}

BroadcastRoomForm.propTypes = {
  broadcastRoom: PropTypes.object,
  categoryId: PropTypes.number.isRequired,
};

BroadcastRoomForm.defaultProps = {
  broadcastRoom: null,
};

export default BroadcastRoomForm;
