import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { IconButton } from "../../atoms";

export const Container = styled.div`
  display: flex;
  position: relative;
  width: 100%;
`;

export const Slide = styled.div`
  position: absolute;
  transition: transform 0.4s ease;
  opacity: 0.4;
  visibility: hidden;


  ${props =>
    props.checked &&
    css`
      z-index: 1;
      left: 50%;
      transform: translateX(-50%);
      cursor: pointer;
      opacity: 1;
      visibility: visible;
    `}

  ${props =>
    props.isNextOne &&
    css`
      left: calc(50% + (${`${props.gap}px`}));
      transform: translateX(calc(50%));
      visibility: visible;
    `}

    ${props =>
      props.isPrevOne &&
      css`
        left: calc(50% - (${`${props.gap}px`}));
        transform: translateX(-150%);
        visibility: visible;
      `}
`;

const WhiteOverLay = styled.div`
  position: absolute;
  top: 0;
  z-index: 2;
  height: 100%;
  width: 20vw;
`;

const SlideButton = styled(IconButton)`
  position: absolute;
  z-index: 3;
  top: 50%;
  transform: translateY(-50%);

  svg {
    height: 30px;
    width: 30px;
    color: ${props => props.theme.colors.accent};
  }
`;

export const PrevButton = styled(SlideButton)`
  left: 0;
`;

export const NextButton = styled(SlideButton)`
  right: 0;
`;

export const LeftOverlay = styled(WhiteOverLay)`
  background: transparent linear-gradient(90deg, #f5f5f5 0%, #f5f5f500 100%) 0%
    0% no-repeat padding-box;
  left: 0;
`;

export const RightOverlay = styled(WhiteOverLay)`
  right: 0;
  background: transparent linear-gradient(90deg, #f5f5f5 0%, #f5f5f500 100%) 0%
    0% no-repeat padding-box;
  transform: matrix(-1, 0, 0, -1, 0, 0);
`;
