import types from "./types";

// LIST
export function listAdminRegistrationFields() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listAdminRegistrationFieldsSuccessful({ registrationFields }) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { registrationFields },
  };
}
export function setRegistrationFields({ registrationFields }) {
  return {
    type: types.LIST.SET,
    payload: {
      registrationFields,
    },
  };
}
export function clearAdminRegistrationFields() {
  return {
    type: types.LIST.CLEAR,
  };
}

// SHOW
export function showUser(registrationFieldId) {
  return {
    type: types.SHOW.REQUEST,
    payload: { registrationFieldId },
  };
}
export function showUserSuccessful(user) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { user },
  };
}
export function clearUser() {
  return {
    type: types.SHOW.CLEAR,
  };
}

// CREATE
export function createRegistrationField({ registrationField }) {
  return {
    type: types.CREATE.REQUEST,
    payload: { registrationField },
  };
}
export function createRegistrationFieldSuccessful({ registrationField }) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { registrationField },
  };
}
export function createRegistrationFieldFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateRegistrationField({
  registrationField,
  registrationFieldId,
}) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { registrationField, registrationFieldId },
  };
}
export function updateRegistrationFieldSuccessful({ registrationField }) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { registrationField },
  };
}
export function updateRegistrationFieldFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteRegistrationField({ registrationFieldId }) {
  return {
    type: types.DELETE.REQUEST,
    payload: { registrationFieldId: Number(registrationFieldId) },
  };
}
export function deleteRegistrationFieldSuccessful({ registrationFieldId }) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { registrationFieldId },
  };
}

export function deleteRegistrationFieldFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// CLEAR
export function clearRegistrationFieldForm() {
  return {
    type: types.FORM.CLEAR,
  };
}

// REORDER
export function reorderRegistrationField({ reorderedList }) {
  return {
    type: types.REORDER.REQUEST,
    payload: { reorderedList },
  };
}
export function reorderRegistrationFieldSuccessful() {
  return {
    type: types.REORDER.SUCCESSFUL,
  };
}
export function reorderRegistrationFieldFailed() {
  return {
    type: types.REORDER.FAILED,
  };
}
