import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { SenderEmailInput } from "~/modules/signed/modules/cms/components/SenderEmailInput";
import {
  ScrollContainer,
  TimesContainer,
  Input,
  TranslateInput,
  LeftContainer,
  RigthContainer,
} from "./styles";

// CUSTOM IMPORTS
import { getFullDate, isToday, getFullHours } from "~/shared/utils/dates";
import { Checkbox } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminEmail } from "../../../../hooks";

function EmailInformation({ sendNow, setSendNow }) {
  // STATES
  const { translate } = useTranslation();
  const { emailVerifyStatus, CHECK_STATUS } = useAdminEmail();

  // FUNCTIONS
  const handleDateInputChange = e => {
    const { value } = e.target;

    if (value)
      if (isToday({ date: value })) {
        const timeInput = document.getElementById("scheduleTime");

        /** Add an min into time input if the date is today */
        if (timeInput) timeInput.setAttribute("min", getFullHours(new Date()));
      }
  };

  function handleCheckboxClick() {
    setSendNow(!sendNow);
  }

  return (
    <ScrollContainer>
      <LeftContainer>
        <TranslateInput
          name="name"
          title={translate("components.input.emailTitle.title")}
          placeholder={translate("components.input.emailTitle.placeholder")}
        />
        <TranslateInput
          name="about"
          title={translate("components.input.emailSubject.title")}
          placeholder={translate("components.input.emailSubject.placeholder")}
        />
        <TranslateInput
          name="description"
          title={translate("components.input.emailDescription.title")}
          placeholder={translate(
            "components.input.emailDescription.placeholder"
          )}
        />
      </LeftContainer>
      <RigthContainer>
        <SenderEmailInput
          variant={emailVerifyStatus === CHECK_STATUS.NOT_VERIFIED && "error"}
          name="sender_email"
          title={translate("components.input.emailSender.title")}
          placeholder={translate("components.input.emailSender.placeholder")}
          style={{ marginBottom: 16 }}
        />
        <Input
          name="sender_name"
          title={translate("components.input.emailSenderName.title")}
          placeholder={translate(
            "components.input.emailSenderName.placeholder"
          )}
        />
        <TimesContainer sendNow={sendNow}>
          <Input
            className="time-input"
            title={translate("components.input.shippingDate.title")}
            id="scheduleDate"
            name="scheduleDate"
            type="date"
            disabled={sendNow}
            min={getFullDate(new Date())}
            onChange={e => handleDateInputChange(e)}
          />

          <Input
            id="scheduleTime"
            className="time-input"
            title={translate("components.input.schedule.title")}
            name="scheduleTime"
            type="time"
            disabled={sendNow}
          />

          <div className="checkbox-area">
            <Checkbox
              checked={sendNow}
              onClick={handleCheckboxClick}
              name="send_now"
              initialValue={sendNow}
              placeholder={translate("common.sendImmediately")}
            />
          </div>
        </TimesContainer>
      </RigthContainer>
    </ScrollContainer>
  );
}

EmailInformation.propTypes = {
  sendNow: PropTypes.bool.isRequired,
  setSendNow: PropTypes.func.isRequired,
};

export default EmailInformation;
