//* CUSTOM IMPORTS
import { isSameDay } from "~/shared/utils/dates";
import { API_STATUS } from "~/shared/utils/apis";
import { meetingTypes } from "./types";

export const MEETINGS_INITIAL_DATA = {
  page: 1,
  data: [],
  lastPage: 1,
  isLoaded: false,
  status: API_STATUS.PENDING,
  itemId: null,
};

const { LIST, PAGINATE, DELETE } = meetingTypes;

/** This function will receive the meetings and joins them into days.  */
const joinMeetingsByDay = meetings => {
  const newArray = [];

  meetings.forEach(scheduledMeeting => {
    const { meeting } = scheduledMeeting;

    if (!meeting) return;

    const { start_date } = meeting;

    if (!newArray.length) {
      newArray.push({ date: start_date, data: [scheduledMeeting] });
    } else {
      const lastIndex = newArray.length - 1;

      if (
        isSameDay({ dateLeft: newArray[lastIndex].date, dateRight: start_date })
      ) {
        newArray[lastIndex].data.push(scheduledMeeting);
      } else {
        newArray.push({ date: start_date, data: [scheduledMeeting] });
      }
    }
  });

  return newArray;
};

export const meetingsReducer = (state, action) => {
  try {
    const { payload, type } = action;

    switch (type) {
      case LIST.REQUEST:
        return {
          ...state,
          isLoaded: false,
          status: API_STATUS.PENDING,
        };

      case LIST.SUCCESS: {
        const joinedMeetings = joinMeetingsByDay(payload.data);

        return {
          ...state,
          ...payload,
          data: joinedMeetings,
          isLoaded: true,
          status: API_STATUS.SUCCESS,
        };
      }

      case PAGINATE.REQUEST:
        return {
          ...state,
          status: API_STATUS.PAGING,
        };

      case PAGINATE.SUCCESS: {
        const lastStates = [];

        state.data.forEach(day => {
          day.data.forEach(dayData => {
            lastStates.push(dayData);
          });
        });

        const joinedMeetings = joinMeetingsByDay([
          ...lastStates,
          ...payload.data,
        ]);

        return {
          isLoaded: true,
          ...payload,
          data: joinedMeetings,
          status: API_STATUS.SUCCESS,
        };
      }

      case DELETE.REQUEST: {
        const { scheduledMeetingId } = payload;

        return {
          ...state,
          status: API_STATUS.DELETING,
          itemId: scheduledMeetingId,
        };
      }

      case DELETE.SUCCESS: {
        const { scheduledMeetingId, startDate } = payload;

        /** Find the day of the meeting to be deleted */
        const findedDayIndex = state.data.findIndex(
          day => day.date === startDate
        );

        if (findedDayIndex !== -1) {
          const findedDay = state.data[findedDayIndex];

          /** Remove the meeting with the scheduledMeetingId */
          const filteredData = findedDay.data.filter(
            scheduledMeeting => scheduledMeeting.id !== scheduledMeetingId
          );

          /** Change the finded day array with new filtered data */
          findedDay.data = filteredData;

          /** If the data of the fined day is empty, remove the finded day of the array. */
          if (!findedDay.data.length) state.data.splice(findedDayIndex, 1);
        }

        return {
          ...state,
          status: API_STATUS.SUCCESS,
          itemId: null,
        };
      }

      default:
        throw new Error(`Unhandled action type ${action.type}`);
    }
  } catch (error) {
    console.log(error);
  }
};
