import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { useParams } from "react-router-dom";
import { unshitItem, deleteItem } from "~/shared/utils/lists";

//* APIS
import {
  getUserData,
  getProfileSettings,
  getSettings,
  updateProfileSettings,
  updateSettings,
  listConnections as callListConnections,
  listUserConnections as callListUserConnections,
  createConnection as callCreateConnection,
  updateConnection as callUpdateConnection,
  listBookmarks,
} from "~/modules/signed/modules/client/apis/profile";

//* CUSTOM IMPORTS
import { CONNECTION_TYPES } from "~/shared/utils/types";
import { openUserProfile } from "~/shared/routes/navigation";
import { ProfileBookmarkProvider } from "./bookmarks";
import { ProfileGamificationProvider } from "./gamification";

const ProfileContext = createContext({});

const CONNECTIONS_INITIAL_DATA = {
  data: [],
  isLoaded: false,
  page: 1,
  lastPage: null,
  isPaging: false,
  isDeleting: false,
};

const ProfileProvider = ({ children }) => {
  const { userId: userIdParam } = useParams();

  // STATES
  // CONNECTION STATES
  const [connectedConnections, setConnectedConnections] = React.useState(
    CONNECTIONS_INITIAL_DATA
  );
  const [pendingConnections, setPendingConnections] = React.useState(
    CONNECTIONS_INITIAL_DATA
  );

  const [disconectedConnections, setDisconectedConnections] = React.useState(
    CONNECTIONS_INITIAL_DATA
  );

  const [otherUserConnections, setOtherUserConnections] = React.useState(
    CONNECTIONS_INITIAL_DATA
  );

  const [commonConnections, setCommonConnections] = React.useState(
    CONNECTIONS_INITIAL_DATA
  );

  const [selectedUser, setSelectedUser] = React.useState({});
  const [isUserLoaded, setIsUserLoaded] = React.useState(false);
  const [profileSettings, setProfileSettings] = React.useState([]);
  const [settings, setSettings] = React.useState([]);
  const [settingsIsLoaded, setSettingsIsLoaded] = React.useState(false);
  const [searchString, setSearchString] = React.useState("");
  const [connectedCurrentPage, setConnectedCurrentPage] = React.useState(1);
  const [pendingCurrentPage, setPendingCurrentPage] = React.useState(1);
  const [otherCurrentPage, setOtherCurrentPage] = React.useState(1);
  const [commonCurrentPage, setCommonCurrentPage] = React.useState(1);
  const [isSearching, setIsSearching] = React.useState(false);
  const [bookmarks, setBookmarks] = React.useState([]);
  const [buttonStatus, setButtonStatus] = React.useState(null);
  const [contactState, setContactState] = React.useState([]);
  const [chatState, setChatState] = React.useState([]);
  const [statusState, setStatusState] = React.useState([]);
  const [isSettingsLoaded, setIsSettingsLoaded] = React.useState(false);

  // FUNCTIONS
  /** callback to populate logged user connections states.
  Uses the status and the correspondent setState */
  const listConnections = React.useCallback(
    (page, status, setState, search) => {
      const list = async () => {
        setIsSearching(true);

        await callListConnections({
          page,
          status,
          search,
          profile_id: selectedUser.id,
        }).then(response => {
          setState(() => {
            return {
              data: response.data.data,
              isLoaded: true,
              page: response.data.page,
              lastPage: response.data.lastPage,
              isPaging: false,
            };
          });
        });

        setState(value => ({ ...value, isPaging: false, isSearching: false }));
        setIsSearching(false);
      };

      list();
    },
    [selectedUser.id]
  );

  /** callback to populate other users connections states */
  const listUserConnections = React.useCallback(
    (page, fetch, setState, search) => {
      const list = async () => {
        setIsSearching(true);
        await callListUserConnections({
          page,
          fetch,
          search,
          profile_id: selectedUser.id,
        }).then(response => {
          setState(() => {
            return {
              data: response.data.data,
              isLoaded: true,
              page: response.data.page,
              lastPage: response.data.lastPage,
              isPaging: false,
            };
          });
        });

        setState(value => ({ ...value, isPaging: false, isSearching: false }));
        setIsSearching(false);
      };

      list();
    },
    [selectedUser.id]
  );

  /** Clear profile infos */
  const clearProfile = React.useCallback(() => {
    setCommonConnections(CONNECTIONS_INITIAL_DATA);
    setConnectedConnections(CONNECTIONS_INITIAL_DATA);
    setPendingConnections(CONNECTIONS_INITIAL_DATA);
    setOtherUserConnections(CONNECTIONS_INITIAL_DATA);
  }, []);

  React.useEffect(() => {
    if (userIdParam) {
      clearProfile();
      setIsUserLoaded(false);
    }
  }, [clearProfile, userIdParam]);

  /** Open a user profile based on ID */
  const profileNavigation = user => {
    clearProfile();
    setIsUserLoaded(false);
    openUserProfile(user.id);
  };

  /** setState based on status props */
  const setConnections = React.useCallback((status, callback) => {
    switch (status) {
      case CONNECTION_TYPES.CONNECTED:
        return setConnectedConnections(value => callback(value));

      case CONNECTION_TYPES.PENDING:
        return setPendingConnections(value => callback(value));

      default:
        return setDisconectedConnections(value => callback(value));
    }
  }, []);

  /** Control the pagination based on type/status props */
  const setPagination = React.useCallback(
    (type, currentPage) => {
      switch (type) {
        case CONNECTION_TYPES.CONNECTED:
          setConnectedConnections(value => ({ ...value, isPaging: true }));
          return setConnectedCurrentPage(currentPage);

        case CONNECTION_TYPES.PENDING:
          setPendingConnections(value => ({ ...value, isPaging: true }));
          return setPendingCurrentPage(currentPage);

        case CONNECTION_TYPES.COMMON:
          setCommonConnections(value => ({ ...value, isPaging: true }));
          return setCommonCurrentPage(currentPage);

        case CONNECTION_TYPES.ALL:
          setOtherCurrentPage(value => ({ ...value, isPaging: true }));
          return otherCurrentPage(currentPage);

        default:
      }
    },
    [otherCurrentPage]
  );

  /** Send a connection request to a user - set the status to "pending" */
  const createConnection = React.useCallback(() => {
    return new Promise(resolve => {
      callCreateConnection({ user_connection_id: selectedUser.id }).then(
        response => {
          setConnectedConnections(CONNECTION_TYPES.PENDING, value => ({
            ...value,
          }));

          resolve(response);
        }
      );
    });
  }, [selectedUser.id]);

  /** Callback to update a connection status
   * can be used to disconect, accep a connection and cancel a request
   */
  const updateConnection = React.useCallback(
    (connectionId, newStatus, oldStatus) => {
      setConnections(oldStatus, value => ({ ...value, isDeleting: true }));
      return new Promise(resolve => {
        callUpdateConnection({
          user_connection_id: connectionId,
          status: newStatus,
        }).then(response => {
          setConnections(oldStatus, value => ({
            ...value,
            data: deleteItem({ itemId: connectionId, list: value.data }),
          }));
          setConnections(response.data.status, value => ({
            ...value,
            data: unshitItem({ newItem: response.data.user, list: value.data }),
          }));
          resolve(response);
        });
      });
    },
    [setConnections]
  );

  /** Paginate control */
  const paginateConnections = React.useCallback(
    (status, page) => {
      setConnections(status, value => ({
        ...value,
        page,
      }));
      setPagination(status, page);
    },
    [setConnections, setPagination]
  );

  /** Populate the user data to be displayed in the profile */
  const openProfile = React.useCallback(userId => {
    setIsUserLoaded(false);

    getUserData(userId)
      .then(({ data }) => {
        setSelectedUser(data);
        setButtonStatus(data.connection);
        setIsUserLoaded(true);
      })
      .catch(() => setIsUserLoaded(false));
  }, []);

  /** List dynamic attributes visibility settings */
  const listProfileSettings = React.useCallback(userId => {
    const get = async () => {
      setSettingsIsLoaded(false);
      await getProfileSettings(userId).then(({ data }) => {
        setProfileSettings(data);
        setSettingsIsLoaded(true);
      });
    };

    get();
  }, []);

  /** List major visibility and contact settings that will be showed in user profile */
  const listSettings = React.useCallback(userId => {
    const get = async () => {
      setIsSettingsLoaded(false);
      await getSettings(userId).then(({ data }) => {
        setSettings(data);
        setContactState(data.contact);
        setChatState(data.chat);
        setIsSettingsLoaded(true);
      });
    };

    if (userId) get();
  }, []);

  /** Popule user settings confing in the privacy modal */
  React.useEffect(() => {
    if (selectedUser) listSettings(selectedUser.id);
  }, [listSettings, selectedUser]);

  /** Real time change dynamic attributes visibility */
  const changeProfileSettings = React.useCallback(
    (userId, data) => {
      const update = async () => {
        setSettingsIsLoaded(false);
        await updateProfileSettings(userId, data).then(() => {
          setSettingsIsLoaded(true);
          listProfileSettings(userId);
        });
      };

      update();
    },
    [listProfileSettings]
  );

  const getBookmarks = React.useCallback((page, search, profileId) => {
    const list = async () => {
      await listBookmarks(page, search, profileId).then(({ data }) => {
        setBookmarks(data);
      });
    };

    list();
  }, []);

  /** Change dynamic attributes visibility */
  const changeSettings = React.useCallback(
    (userId, data) => {
      const update = async () => {
        await updateSettings(userId, data).then(() => {
          listSettings(userId);
        });
      };
      update();
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <ProfileContext.Provider
      value={{
        // STATES
        selectedUser,
        isUserLoaded,
        profileSettings,
        settings,
        settingsIsLoaded,
        connectedConnections,
        pendingConnections,
        commonConnections,
        disconectedConnections,
        otherUserConnections,
        connectedCurrentPage,
        pendingCurrentPage,
        commonCurrentPage,
        otherCurrentPage,
        isSearching,
        bookmarks,
        searchString,
        buttonStatus,
        contactState,
        chatState,
        statusState,
        isSettingsLoaded,

        // FUNCTIONS
        listConnections,
        openProfile,
        listProfileSettings,
        listSettings,
        changeProfileSettings,
        paginateConnections,
        setSearchString,
        createConnection,
        updateConnection,
        profileNavigation,
        setConnectedConnections,
        setPendingConnections,
        setCommonConnections,
        setOtherUserConnections,
        setIsSearching,
        getBookmarks,
        listUserConnections,
        changeSettings,
        setButtonStatus,
        setContactState,
        setChatState,
        setStatusState,
      }}
    >
      <ProfileGamificationProvider>
        <ProfileBookmarkProvider>{children}</ProfileBookmarkProvider>
      </ProfileGamificationProvider>
    </ProfileContext.Provider>
  );
};

ProfileProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useProfile = () => {
  const context = useContext(ProfileContext);

  if (!context)
    throw new Error("useProfile must be used within ProfileProvider");

  return context;
};

export { ProfileProvider, useProfile };
