import styled, { css, keyframes } from "styled-components";
import { shade, lighten } from "polished";

const renderColor = (color, accent) => {
  if (!color) return accent;

  return color;
};

const animation = keyframes`
  from{
    box-shadow: 0px 0px 6px #e8414188;
    color: #ffffff54;
  }

  to{
    box-shadow: 0px 0px 18px #ffffff;
    color: #fff;
  }
`;

export const TagContainer = styled.div`
  display: flex;
  width: fit-content;
  border-radius: ${props => props.borderRadius}px;
  padding: 4px 15px;
  margin-right: 5px;
  color: ${props => props.theme.colors.surface};
  font-size: 10px;
  background: ${props => renderColor(props.color, props.theme.colors.accent)};
  font-weight: bold;
  max-width: 300px;
  pointer-events: ${props => (props.hoverable ? "unset" : "none")};
  cursor: ${props => props.hoverable && "pointer"};

  p {
    margin: 0 auto;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  ${props =>
    props.withBoxShadow &&
    css`
      box-shadow: 0px 3px 5px ${props.theme.colors.shadow};
    `}

  &:hover {
    background-color: ${props =>
      shade(0.15, renderColor(props.color, props.theme.colors.accent))};
  }

  ${props =>
    props.size === "large" &&
    css`
      padding: 8px 20px;
      font-size: 12px;
    `};

  ${props =>
    props.isSelected &&
    css`
      background-color: ${props.theme.colors.accent};
    `}

  ${props => {
    const { variant, theme, isSelected } = props;
    const { colors } = theme;

    switch (variant) {
      case "opaque":
        return css`
          background-color: ${isSelected
            ? colors.smoked
            : lighten(0.7, colors.almostSmoked)};
          color: ${isSelected ? colors.surface : colors.smoked};
          border: 1px solid ${colors.smoked};
          transition: background-color 0.3s ease;

          &:hover {
            background-color: ${colors.smoked};
            color: ${colors.surface};
          }
        `;

      case "outlined":
        return css`
          background-color: ${isSelected
            ? colors.accent
            : lighten(0.39, colors.accent)};
          color: ${isSelected ? colors.surface : colors.accent};
          border: 1px solid ${colors.accent};
          transition: background-color 0.3s ease;

          &:hover {
            background-color: ${colors.accent};
            color: ${colors.surface};
          }
        `;

      case "live":
        return css`
          border-radius: 4px;
          background: ${isSelected ? shade(0.3, "#e84141") : "#e84141"};
          animation: ${animation} 1s infinite alternate-reverse;

          &:hover {
            background: #e84141;
          }
        `;

      default:
        break;
    }
  }}

${props => {
  const { format } = props;

  switch (format) {
    case "squared":
    case "circled":
      return css`
        border-radius: ${format === "circled" ? "50%" : "4px"};
        padding: 8px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:before {
          content: "";
          display: block;
          padding-top: 100%;
        }
      `;

    default:
      break;
  }
}}`;
