import React from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { MdChat, BsSquare } from "react-icons/all";

//* ACTIONS
import {
  activeTheaterMode,
  deactiveTheaterMode,
} from "~/modules/signed/modules/client/store/rooms/actions";

//* CUSTOM IMPORTS
import { isListValid } from "~/shared/utils/validations";
import { Container, Player, Chat } from "./styles";

function PlayerContainer({
  className,
  videoUrl,
  type,
  extraPlayers,
  chatUrl,
  isTheaterModeActived,
  noChat,
  room,
  broadcastRoomId,
  disableChatController,
}) {
  //* REDUCER
  const dispatch = useDispatch();

  //* STATES
  const [isChatVisible, setIsChatVisible] = React.useState(!noChat);

  //* FUNCTIONS
  const onTheaterModeButtonClick = () => {
    if (isTheaterModeActived) {
      dispatch(deactiveTheaterMode());
    } else {
      dispatch(activeTheaterMode());

      //* *  Scroll window to top after resize to theater mode */
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  };

  const playerControllers = [
    {
      id: 1,
      name: "Modo Teatro",
      icon: <BsSquare />,
      onClick: onTheaterModeButtonClick,
    },
  ];

  if (!noChat && !disableChatController)
    playerControllers.push({
      id: 3,
      name: "Esconder Chat",
      icon: <MdChat />,
      onClick: () => setIsChatVisible(!isChatVisible),
    });

  return (
    <Container
      hasExtraPlayers={isListValid(extraPlayers)}
      className={className}
      isTheaterModeActived={isTheaterModeActived}
      isChatVisible={isChatVisible}
    >
      <Player
        isTheaterModeActived={isTheaterModeActived}
        broadcastRoomId={broadcastRoomId}
        room={room}
        className="broadcast-room-player"
        src={videoUrl}
        type={type}
        extraPlayers={extraPlayers}
        controllers={playerControllers}
      />

      {!noChat && (
        <Chat
          className="broadcast-room-chat"
          broadcastRoomId={room.id}
          src={chatUrl}
        />
      )}
    </Container>
  );
}

PlayerContainer.propTypes = {
  videoUrl: PropTypes.string.isRequired,
  chatUrl: PropTypes.string,
  room: PropTypes.object,
  className: PropTypes.string,
  isTheaterModeActived: PropTypes.any.isRequired,
  extraPlayers: PropTypes.arrayOf(PropTypes.object),
  type: PropTypes.string.isRequired,
  noChat: PropTypes.bool,
  broadcastRoomId: PropTypes.number.isRequired,
  disableChatController: PropTypes.bool.isRequired,
};

PlayerContainer.defaultProps = {
  chatUrl: null,
  room: null,
  className: null,
  extraPlayers: [],
  noChat: false,
};

export default PlayerContainer;
