import React from "react";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  createRegistrationField,
  reorderRegistrationField,
} from "~/modules/signed/modules/cms/modules/admin/store/registrationFields/actions";

//* CUSTOM IMPORTS
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { Trans } from "~/shared/components/atoms";
import { DialogModal } from "~/shared/components/molecules";
import { predefinedFields, customFields } from "~/shared/utils/inputs";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import {
  openRegistrationFieldModal,
  openAdminBroadcastRooms,
  openAdminSettings,
} from "~/shared/routes/navigation";
import FieldTags from "./FieldsTags";
import RegistrationFieldList from "~/modules/signed/modules/cms/components/lists/RegistrationFieldList";
import {
  Container,
  LeftContainer,
  RigthContainer,
  Title,
  Footer,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function RegistrationFields() {
  const dispatch = useDispatch();
  const { toJsonTranslation } = useDynamicTranslation();
  const { data: registrationFields, isReordering } = useSelector(
    state => state.adminRegistrationFields.list
  );

  //* STATES
  const [showSaveModal, setShowSaveModal] = React.useState(false);
  const [showCancelModal, setShowCancelModal] = React.useState(false);

  //* FUNCTIONS
  const { translate } = useTranslation();

  const { setBreadCrumbs, setEditMode } = useCMS();

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("common.general"),
        function: openAdminSettings,
      },
      {
        id: 2,
        name: translate("entities.registrationFields.configure"),
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  function handlePredefinedFieldClick(registrationField) {
    const { title, placeholder } = registrationField;

    dispatch(
      createRegistrationField({
        registrationField: {
          ...registrationField,
          title: toJsonTranslation(title),
          placeholder: toJsonTranslation(placeholder),
          order: 0,
        },
      })
    );
  }

  function handleCustomFieldClick(registrationField) {
    openRegistrationFieldModal(registrationField);
  }

  function handleSave() {
    const reorderedList = registrationFields.map((field, index) => {
      return {
        id: Number(field.id),
        order: index,
      };
    });

    dispatch(reorderRegistrationField({ reorderedList }));
  }

  React.useMemo(() => {
    if (isReordering === "FINISH") setShowSaveModal(false);
  }, [isReordering]);

  return (
    <>
      <Container>
        <LeftContainer>
          <Title>
            <Trans translationKey="common.addToForm">
              Adicionar ao formulário
            </Trans>
          </Title>
          <FieldTags
            title={translate("entities.registrationFields.predefined")}
            fieldTags={predefinedFields}
            onClick={handlePredefinedFieldClick}
          />

          <FieldTags
            title={translate("entities.registrationFields.custom")}
            fieldTags={customFields}
            style={{ marginTop: 48 }}
            onClick={handleCustomFieldClick}
          />
        </LeftContainer>

        <RigthContainer>
          <Title>
            <Trans translationKey="entities.participant.data">
              Dados do participante
            </Trans>
          </Title>

          <RegistrationFieldList />

          <Footer
            type="button"
            onCancel={() => setShowCancelModal(true)}
            onClick={() => setShowSaveModal(true)}
          />
        </RigthContainer>
      </Container>

      {/* MODALS */}
      <DialogModal
        title={translate(
          "pages.adminSettingsRegistrationFields.saveModal.title"
        )}
        description={translate(
          "pages.adminSettingsRegistrationFields.saveModal.description"
        )}
        show={showSaveModal}
        onClose={() => setShowSaveModal(false)}
        onCancel={() => setShowSaveModal(false)}
        onConfirm={handleSave}
        isLoading={isReordering === "PENDING"}
      />

      <DialogModal
        title={translate(
          "pages.adminSettingsRegistrationFields.cancelModal.title"
        )}
        description={translate(
          "pages.adminSettingsRegistrationFields.cancelModal.description"
        )}
        show={showCancelModal}
        onClose={() => setShowCancelModal(false)}
        onCancel={() => setShowCancelModal(false)}
        onConfirm={openAdminBroadcastRooms}
      />
    </>
  );
}

export default RegistrationFields;
