import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { useTranslation } from "../../../hooks/translation";
import { Icon } from "../../atoms";
import {
  RingButton,
  Container,
  FadeContainer,
  NotificationList,
  ArrowUp,
  NotificationBackdrop,
} from "./styles";

const notificationButtonPropTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  /** Render a dot icon in the button if TRUE */
  hasNew: PropTypes.bool,
  /** Variant of the button "outlined" "contained" "primary". Default is primary */
  buttonVariant: PropTypes.string,
  /** Callback triggered when <b>NotificationList</b> is closed */
  onReadAll: PropTypes.func,
};

const notificationButtonDefaultPropTypes = {
  className: null,
  style: null,
  hasNew: false,
  buttonVariant: "primary",
  onReadAll: () => {},
};

/** NotificationButton implements all props of <b>NotificationList</b>, so to see
 * all props see <b>Molecules/NotificationList</b>.
 */
const NotificationButton = ({
  className,
  hasNew,
  style,
  buttonVariant,
  onReadAll,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();
  const [isFading, setIsFading] = React.useState(false);
  const [show, setShow] = React.useState(false);

  function handleClick() {
    if (show) {
      setIsFading(true);
      setTimeout(() => {
        setShow(!show);
        onReadAll();
      }, 500);
    } else {
      setShow(!show);
    }
    setTimeout(() => {
      setIsFading(false);
    }, 500);
  }

  return (
    <Container className={className} style={style}>
      <RingButton
        onClick={() => handleClick()}
        variant={buttonVariant}
        tip={translate("entities.notifications.plural")}
      >
        <Icon
          iconName="notifications"
          variant="white"
          hasNew={hasNew}
          badgeOutlineColor={colors.primary}
        />
      </RingButton>

      {show && (
        <>
          <FadeContainer isFading={isFading}>
            <ArrowUp className="arrow-up" />

            <NotificationList {...rest} />
          </FadeContainer>
          <NotificationBackdrop open={show} onClick={handleClick} />
        </>
      )}
    </Container>
  );
};

NotificationButton.propTypes = notificationButtonPropTypes;
NotificationButton.defaultProps = notificationButtonDefaultPropTypes;

export {
  NotificationButton,
  notificationButtonPropTypes,
  notificationButtonDefaultPropTypes,
};
