import React from "react";
import PropTypes from "prop-types";
import { AutoSizer, CellMeasurer, CellMeasurerCache } from "react-virtualized";

//* CUSTOM IMPORTS
import { Container, List } from "./styles";

const virtualizedListPropTypes = {
  /** Accepts any object */
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  /** Callback to render the cell based on data props, return the current data cell value
   * and the index
   */
  renderCell: PropTypes.func,

  emptyComponent: PropTypes.element,

  /** Callback that is triggered when user scroll to the bototm of the <b>List</b> */
  onBottomReached: PropTypes.func,

  /** Callback that is triggered when user scroll to the top of the <b>List</b> */
  onTopReached: PropTypes.func,

  /** If TRUE remove disable the handleScroll function, default is "false" */
  disableScrollListener: PropTypes.bool,

  /** Render an Component in the above the List */
  headerComponent: PropTypes.element,

  /** Render an Component in the first cell at the List */
  firstComponent: PropTypes.element,

  /** Render an Component in the last cell at the List */
  footerComponent: PropTypes.element,

  /** An external ref to control the list  */
  listRef: PropTypes.shape({ current: PropTypes.any }),

  /** If you want an custom CellMeasurerCache */
  cache: PropTypes.shape({
    clearAll: PropTypes.func,
    rowHeight: PropTypes.number,
    fixedWidth: PropTypes.bool,
  }),
};

const virtualizedListDefaultProps = {
  renderCell: () => {},
  onTopReached: () => {},
  onBottomReached: () => {},
  emptyComponent: null,
  disableScrollListener: false,
  headerComponent: null,
  footerComponent: null,
  firstComponent: null,
  listRef: null,
  cache: null,
};

const innerCache = new CellMeasurerCache({
  fixedWidth: true,
  defaultHeight: 40,
});

/** This component is commonly used when you need to render a BIG list
 *
 * VirtualizedList use react-virtualized see more here: https://github.com/bvaughn/react-virtualized
 *
 * <i>*Obs: Need to be inside a flex div</i>
 */
const VirtualizedList = ({
  listRef,
  data,
  renderCell,
  emptyComponent,
  onBottomReached,
  onTopReached,
  disableScrollListener,
  headerComponent,
  firstComponent,
  footerComponent,
  cache,
  ...rest
}) => {
  const rowCount = data.length;

  //* STATES
  const [top, setTop] = React.useState(0);

  React.useMemo(() => {
    if (cache) {
      cache.clearAll();
    } else {
      innerCache.clearAll();
    }
  }, [cache]);

  //* FUNCTIONS
  const handleScroll = element => {
    if (disableScrollListener) return;

    const { clientHeight, scrollHeight, scrollTop } = element;

    if (scrollTop === 0) {
      if (top > 0) {
        onTopReached();
      } else {
        setTop(1);
      }
    }

    //* Set flag if user is in bottom of the list or not
    if (scrollHeight - scrollTop === clientHeight) onBottomReached();
  };

  const rowRenderer = ({ index, key, style, parent }) => {
    if (!data[index]) return null;

    return (
      <CellMeasurer
        cache={cache || innerCache}
        columnIndex={0}
        key={key}
        parent={parent}
        rowIndex={index}
      >
        {({ measure, registerChild }) => (
          <div ref={registerChild} style={style} key={key}>
            {index === 0 && firstComponent}

            {renderCell(data[index], index, measure)}

            {index === data.length - 1 && footerComponent}
          </div>
        )}
      </CellMeasurer>
    );
  };

  return (
    <Container className="virtualized-list">
      {headerComponent}

      {data.length > 0 ? (
        <AutoSizer>
          {({ width, height }) => (
            <List
              {...rest}
              ref={ref => {
                if (listRef) listRef.current = ref;
              }}
              height={height}
              rowCount={rowCount + 1}
              rowHeight={cache ? cache.rowHeight : innerCache.rowHeight}
              deferredMeasurementCache={cache || innerCache}
              rowRenderer={rowRenderer}
              width={width}
              onScroll={handleScroll}
            />
          )}
        </AutoSizer>
      ) : (
        emptyComponent
      )}
    </Container>
  );
};

VirtualizedList.propTypes = virtualizedListPropTypes;

VirtualizedList.defaultProps = virtualizedListDefaultProps;

export {
  VirtualizedList,
  virtualizedListPropTypes,
  virtualizedListDefaultProps,
};
