import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container } from "./styles";
import { Line, Typography, Icon } from "~/shared/components/atoms";
import { SwipeTabs } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";
import { useWindow } from "~/shared/hooks/window";

// TAB FRAGMENTS
import {
  BroadcastRooms,
  VirtualFair,
  Posters,
  Schedule,
  Speakers,
  NetworkRooms,
} from "./components";

const TABS = {
  BROADCAST_ROOMS: 0,
  NETWORK_ROOMS: 1,
  MARKET_ROOMS: 2,
  SPEAKERS: 3,
  SCHEDULE: 4,
  POSTERS: 5,
};

const BookmarkTab = ({ callIf }) => {
  const { windowWidth } = useWindow();
  const { translate } = useTranslation();
  const { setSearchString } = useBookmarkProfile();

  //* STATES
  const [tabIndex, setTabIndex] = React.useState(TABS.BROADCAST_ROOMS);

  // TABS
  const tabs = [
    {
      id: 1,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.broadcastRooms.plural")}
        </Typography>
      ),
    },
    {
      id: 2,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.networkRooms.plural")}
        </Typography>
      ),
    },

    {
      id: 3,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.marketRooms.booths")}
        </Typography>
      ),
    },

    {
      id: 4,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.speakers.plural")}
        </Typography>
      ),
    },

    {
      id: 5,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.schedule.singular")}
        </Typography>
      ),
    },

    {
      id: 6,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.posters.plural")}
        </Typography>
      ),
    },
  ];

  const mobileTabs = [
    {
      id: 1,
      icon: <Icon iconName="broadcastRooms" color="smoked" />,
    },
    {
      id: 2,
      icon: <Icon iconName="networkRooms" color="smoked" />,
    },

    {
      id: 3,
      icon: <Icon iconName="marketRooms" color="smoked" />,
    },

    {
      id: 4,
      icon: <Icon iconName="speakers" color="smoked" />,
    },

    {
      id: 5,
      icon: <Icon iconName="date" color="smoked" />,
    },

    {
      id: 6,
      icon: <Icon iconName="paragraph" color="smoked" />,
    },
  ];

  const handleOnTabChange = index => {
    setTabIndex(index);
    setSearchString("");
  };

  return (
    <>
      {callIf && (
        <Container className="mobile-hide">
          <Line style={{ marginTop: 16, marginBottom: 16 }} />
          <Typography className="title accent" style={{ marginBottom: 16 }}>
            {translate("entities.profile.body.bookmark.plural")}
          </Typography>
          <SwipeTabs
            variant={windowWidth >= 600 ? "regular" : "fullWidth"}
            controlled
            index={tabIndex}
            onChange={handleOnTabChange}
            childrenStyle={{ overflow: "hidden", padding: "8px" }}
            size="small"
            alignment="left"
            tabs={windowWidth >= 600 ? tabs : mobileTabs}
          >
            <BroadcastRooms isFetcheable={tabIndex === TABS.BROADCAST_ROOMS} />

            <NetworkRooms isFetcheable={tabIndex === TABS.NETWORK_ROOMS} />

            <VirtualFair isFetcheable={tabIndex === TABS.MARKET_ROOMS} />

            <Speakers isFetcheable={tabIndex === TABS.SPEAKERS} />

            <Schedule isFetcheable={tabIndex === TABS.SCHEDULE} />

            <Posters isFetcheable={tabIndex === TABS.POSTERS} />
          </SwipeTabs>
        </Container>
      )}
    </>
  );
};

BookmarkTab.propTypes = {
  callIf: PropTypes.bool,
};

BookmarkTab.defaultProps = {
  callIf: false,
};

export { BookmarkTab };
