import React from "react";

import { useParams } from "react-router-dom";

// APIs
import { showPoster } from "~/modules/signed/modules/client/apis/posters";

// CUSTOM IMPORTS
import { useUser } from "~/modules/signed/modules/client/hooks";
import { Spinner } from "~/shared/components/atoms";
import PosterDetailsFragment from "./PosterDetailsFragment";

function PosterDetails() {
  // PARAMS
  const { posterId } = useParams();
  const { setHeaderTitle } = useUser();

  // STATES
  const [poster, setPoster] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);

  // FUNCTIONS
  React.useEffect(() => {
    const show = async () => {
      await showPoster({ posterId }).then(response => {
        setPoster(response.data);
        setIsLoaded(true);
      });
    };

    show();

    return () => {
      setHeaderTitle("");
    };
  }, [posterId, setHeaderTitle]);

  if (isLoaded) {
    setHeaderTitle(poster.title);
  }

  return !isLoaded ? (
    <Spinner centered />
  ) : (
    <PosterDetailsFragment poster={poster} />
  );
}

export default PosterDetails;
