import api from "~/shared/services/api";

export const listEmails = ({ page, type }) =>
  Promise.resolve(
    api.get("/admin/emails", {
      params: {
        page,
        type,
      },
    })
  );

export const showEmail = ({ id }) =>
  Promise.resolve(api.get(`/admin/emails/${id}`));

export const createEmail = ({ data }) =>
  Promise.resolve(api.post("/admin/emails", data));

export const deleteEmail = ({ id }) =>
  Promise.resolve(api.delete(`/admin/emails/${id}`));

export const updateEmail = ({ data, id }) =>
  Promise.resolve(api.put(`/admin/emails/${id}`, data));

export const updateEmailHeader = ({ id, data }) =>
  Promise.resolve(api.put(`/admin/emails/header/${id}`, data));

export const emailLog = ({ id, page = 1 }) =>
  Promise.resolve(api.get(`/admin/emails/logs/${id}?page=${page}`));

export const emailLogCount = ({ id }) =>
  Promise.resolve(api.get(`/admin/emails/logs/count/${id}`));

export const sendVeriticationEmail = ({ email }) =>
  Promise.resolve(api.post(`/admin/emails/ses/add`, { email }));

export const checkEmailVerified = ({ email }) =>
  Promise.resolve(api.get(`/admin/emails/ses/check`, { params: { email } }));

export const emailPreview = ({ header_url, text, currentLanguage }) =>
  Promise.resolve(
    api.post("/admin/emails/preview/html", {
      text,
      header_url,
      current_language: currentLanguage,
    })
  );
