/* eslint-disable consistent-return */
import styled, { css } from "styled-components";
import { leftIn, leftOut } from "../../../../styles/animations";
import { LinkButton } from "../../../atoms/LinkButton";

//* CUSTOM IMPORTS
import {
  Card as CardC,
  Typography,
  Icon as IconC,
  IconButton as IconButtonC,
} from "../../../atoms";

export const Card = styled(CardC)`
  cursor: pointer;
  position: relative;
  padding: 16px 50px;
  width: 320px;
  animation: ${leftIn} 1.2s;

  ${props => {
    if (props.isClosing)
      return css`
        animation: ${leftOut} 2s ease;
      `;
  }}
`;

export const NotificationIcon = styled(IconC)`
  height: 16px;
  width: 16px;
  background-color: ${props => props.color};

  position: absolute;
  top: 0;
  left: 0;
  margin-top: 16px;
  margin-left: 16px;
`;

export const IconButton = styled(IconButtonC)`
  background-color: ${props => props.color};
  position: absolute;
  top: 0;
  right: 0;
`;

export const Title = styled(Typography)`
  margin-bottom: 8px;
  font: normal normal bold 12px/15px Lato;
  text-transform: uppercase;
  color: ${props => props.theme.colors.dark};
  font-weight: bold;
  text-align: left;
`;

export const Description = styled(Typography)`
  font-size: 12px;
  margin-bottom: 4px;
  color: ${props => props.theme.colors.regular};
`;

export const UnreadLink = styled(LinkButton)`
  color: ${props => props.theme.colors.accent};
  font-size: 12px;
  font-weight: bold;
`;

export const Close = styled(IconButton)`
  margin-top: 4px;
  font-size: 12px;
  color: ${props => props.theme.colors.regular};
  opacity: 0.8;
  position: absolute;
  top: 0px;
  right: 0px;
`;

export const ProgressBarContainer = styled.div`
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  bottom: 3px;

  .progressBar {
    border-radius: 0 0 8px 8px;
  }
`;
