import React from "react";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { getFullHours, getFullDate } from "~/shared/utils/dates";
import {
  createSchedule,
  updateSchedule,
} from "~/modules/signed/modules/cms/modules/admin/store/schedules/actions";
import {
  Container,
  Form,
  Input,
  TranslateInput,
  SpeakerCheckList,
  Footer,
} from "./styles";
import { isListValid } from "~/shared/utils/validations";

import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function CreateSchedule({ onClose, schedule, broadcastRoomId, editMode }) {
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  const dispatch = useDispatch();
  const scheuldeReducer = useSelector(state => state.adminSchedule.list);
  const { isCreating, isUpdating } = scheuldeReducer && scheuldeReducer;

  // STATE
  const [showSaveModal, setShowSaveModal] = React.useState(false);
  const [formData, setFormData] = React.useState({});
  const [selectedSpeakers, setSelectedSpeakers] = React.useState([]);
  const [visibility, setVisibility] = React.useState(true);
  const [inputTitle, setInputTitle] = React.useState("");

  // FUNCTIONS
  React.useEffect(() => {
    function fillFormData() {
      const {
        title,
        start_time: startTime,
        end_time: endTime,
        speakers,
      } = schedule;

      setInputTitle(title);

      setFormData({
        ...schedule,
        date: getFullDate(startTime),
        startTime: getFullHours(startTime),
        endTime: getFullHours(endTime),
      });

      if (isListValid(speakers)) {
        const speakerIds = speakers.map(speaker => speaker.id);
        setSelectedSpeakers(speakerIds);
      }
    }

    if (editMode) {
      fillFormData();
    } else {
      setFormData({});
      setInputTitle("");
    }

    return () => {
      function clearForm() {
        setFormData({});
        setSelectedSpeakers([]);
      }

      clearForm();
    };
  }, [editMode, schedule]);

  function dispatchSubmit() {
    const scheduleFormData = {
      ...formData,
      speakersIds: selectedSpeakers,
    };

    if (editMode) {
      dispatch(updateSchedule(scheduleFormData, broadcastRoomId, schedule.id));
    } else {
      dispatch(createSchedule(scheduleFormData, broadcastRoomId));
    }
  }

  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH") {
      setVisibility(false);
      setShowSaveModal(false);

      setTimeout(onClose, 500);
    }
  }, [isCreating, isUpdating, onClose]);

  return (
    <Container id="fff" visibility={visibility}>
      <Form
        onSubmit={data => {
          setFormData(data);
          setShowSaveModal(true);
        }}
        yupValidation={{
          title: Yup.string().required(
            translate("entities.broadcastRooms.yupValidationTitle")
          ),
          date: Yup.string().required(
            translate("entities.broadcastRooms.yupValidationDate")
          ),
          startTime: Yup.string().required(
            translate("entities.broadcastRooms.yupValidationStartTime")
          ),
          endTime: Yup.string().required(
            translate("entities.broadcastRooms.yupValidationEndTime")
          ),
        }}
        initialData={formData}
      >
        <TranslateInput
          value={inputTitle}
          onChange={e => setInputTitle(e.target.value)}
          name="title"
          title={translate("entities.broadcastRooms.inputScheduleTitle")}
          placeholder={translate(
            "entities.broadcastRooms.inputSchedulePlaceholder"
          )}
        />

        <TranslateInput
          isTextArea
          name="description"
          title={translate(
            "entities.broadcastRooms.inputScheduleDescriptionTitle"
          )}
          placeholder={translate(
            "entities.broadcastRooms.inputScheduleDescriptionPlaceholder"
          )}
        />

        <div className="create-schedule-times">
          <div style={{ position: "relative", zIndex: 1 }}>
            <Input
              name="date"
              title={translate("entities.broadcastRooms.inputTitleStartDate")}
              type="date"
            />
            <Input
              name="startTime"
              title={translate("entities.broadcastRooms.inputTitleStartTime")}
              type="time"
            />
            <Input
              name="endTime"
              title={translate("entities.broadcastRooms.inputTitleEndTime")}
              type="time"
            />
          </div>

          <SpeakerCheckList
            selectedSpeakers={selectedSpeakers}
            setSelectedSpeakers={setSelectedSpeakers}
          />
        </div>

        <footer id="create-schedule-footer">
          <Footer
            disableCancelButton
            entity={translate("entities.broadcastRooms.scheduleFooter")}
            entityName={dTranslate(formData.title)}
            onConfirm={dispatchSubmit}
            modalType={editMode ? "update" : "create"}
            showModal={showSaveModal}
            isLoading={isUpdating === "PENDING" || isCreating === "PENDING"}
          />
        </footer>
      </Form>
    </Container>
  );
}

CreateSchedule.propTypes = {
  onClose: PropTypes.func,
  schedule: PropTypes.object,
  broadcastRoomId: PropTypes.number.isRequired,
  editMode: PropTypes.bool.isRequired,
};

CreateSchedule.defaultProps = {
  onClose: () => {},
  schedule: null,
};

export default CreateSchedule;
