import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Icon } from "../../atoms";
import { simpleMenuPropTypes } from "..";
import { Touchable, Card, DefaultMenu, CustomMenu } from "./styles";

const simpleCellPropTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,

  variant: PropTypes.oneOf(["default", "accent"]),

  /** Callback triggered when edit button is clicked */
  onEdit: PropTypes.func,

  /** Callback triggered when delete is confirmed */
  onDelete: PropTypes.func,

  /** String name of entity */
  entityName: PropTypes.string,

  /** String title for entity */
  entity: PropTypes.string,

  /** render an component at top of this component */
  headerComponent: PropTypes.element,

  /** render an component at bottom of this component */

  footerComponent: PropTypes.element,
  /** By default <b>SimpleCell</b> already has an <b>DefaultMenu</b>, that contains
   * 2 options, the edit and the delete options.But if this props has implemented
   * will render an <b>CustomMenu</b> instead of <b>DefaultMenu</b>.
   */
  simpleMenu: PropTypes.shape(simpleMenuPropTypes),

  /** Callback that is triggered when users click into <b>Card</b> */
  onClick: PropTypes.func,
};

const simpleCellDefaultProps = {
  onEdit: () => {},
  onDelete: () => {},
  onClick: () => {},

  entityName: null,
  entity: null,
  headerComponent: null,
  footerComponent: null,
  simpleMenu: null,

  variant: "default",
};

/** Just an Simple cell, with the <b>CRUDOptions</b>  component. */
function SimpleCell({
  onEdit,
  onDelete,
  entityName,
  entity,
  simpleMenu,
  onClick,
  children,
  variant,
  ...rest
}) {
  return (
    <Touchable className="simple-cell-touchable" variant={variant} {...rest}>
      <Card className="simple-cell-card" onClick={onClick}>
        {children}
      </Card>

      {simpleMenu ? (
        <CustomMenu
          className="simple-cell-menu"
          {...simpleMenu}
          buttonIcon={<Icon iconName="menu" />}
        />
      ) : (
        <DefaultMenu
          className="simple-cell-menu"
          entity={entity}
          entityName={entityName}
          onEdit={onEdit}
          onDelete={onDelete}
        />
      )}
    </Touchable>
  );
}

SimpleCell.propTypes = simpleCellPropTypes;

SimpleCell.defaultProps = simpleCellDefaultProps;

export { SimpleCell, simpleCellPropTypes, simpleCellDefaultProps };
