import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import CellLoader from "../../../molecules/cellLoaders/ChatMessageCellLoader";
import { ReactComponent as LikeEmoji } from "../../../../assets/static/likeEmoji.svg";
import { Typography, Avatar, Box, Icon } from "../../../atoms";
import {
  ReplyMessageCard,
  replyMessageCardPropTypes,
} from "../../../molecules";
import { isToday, getFullDate, getFullHours } from "../../../../utils/dates";
import {
  Card,
  LikeButton,
  SimpleMenu,
  Message,
  ReplyButton,
  Container,
} from "./styles";

const propTypes = {
  /** An image url to render the avatar  */
  avatar: PropTypes.oneOfType([null, PropTypes.string]),

  /** The message to be displayed in the component */
  message: PropTypes.string.isRequired,

  /** Render an date */
  createdAt: PropTypes.string,

  /** The bold part of the message */
  messageTitle: PropTypes.string.isRequired,

  /** The number of likes to be displayed, default is 0 */
  likes: PropTypes.oneOfType([null, PropTypes.number]),

  /** If TRUE, change the LikeButton styles */
  isLiked: PropTypes.boold,

  /** An Callback that is triggered when user click into ReplyButton */
  onReplyClick: PropTypes.func,

  /** An Callback that is triggered when user click into LikeButton */
  onLikeButtonClick: PropTypes.func,

  /** <b>SimpleMenu</b> props */
  simpleMenu: PropTypes.object.isRequired,

  /** If TRUE, show an loader instead of the default component */
  isLoading: PropTypes.bool,

  /** Check if the user is ADMIN, if TRUE, add a highlight to the message */
  hasHighlight: PropTypes.bool,

  style: PropTypes.any,

  /** If <b>true</b> disable <b>ReplyButton<b/> */
  disableReply: PropTypes.bool,

  replyMessageCard: PropTypes.shape({ replyMessageCardPropTypes }),
};

const defaultProps = {
  avatar: null,
  likes: 0,
  isLiked: false,
  onReplyClick: () => {},
  onLikeButtonClick: () => {},
  isLoading: false,
  disableReply: false,
  style: null,
  createdAt: null,
  replyMessageCard: null,
  hasHighlight: false,
};

function ChatMessageCell({
  avatar,
  message,
  messageTitle,
  createdAt,
  likes,
  isLiked,
  onLikeButtonClick,
  simpleMenu,
  style,
  isLoading,
  onReplyClick,
  replyMessageCard,
  hasHighlight,
  disableReply,
  ...rest
}) {
  if (isLoading)
    return <CellLoader style={{ height: 66, ...style }} height={66} />;

  return (
    <Container hasHighlight={hasHighlight}>
      <Card style={style} {...rest}>
        {replyMessageCard && (
          <ReplyMessageCard
            {...replyMessageCard}
            style={{ marginBottom: 8 }}
            disableCloseButton
          />
        )}

        <Box display="flex">
          <Avatar src={avatar} size={26} style={{ marginRight: 8 }} />

          <Message hasHighlight={hasHighlight}>
            <>
              <strong style={{ marginRight: 4 }}>{messageTitle}</strong>
              {message}
            </>
          </Message>
        </Box>

        {createdAt && (
          <Typography style={{ marginLeft: 32 }} className="opaque small">
            {`${getFullHours(createdAt)}`}
            {!isToday({ date: new Date() }) &&
              ` - ${getFullDate(createdAt, true)}`}
          </Typography>
        )}

        <LikeButton
          className="like-button"
          isLiked={isLiked}
          onClick={onLikeButtonClick}
          isVisible={likes > 0}
        >
          <LikeEmoji />

          <Typography className="bold" style={{ marginLeft: 8 }}>
            {likes}
          </Typography>
        </LikeButton>

        {!disableReply && (
          <ReplyButton
            className="accent uppercase small bold reply-button"
            onClick={onReplyClick}
          >
            RESPONDER
          </ReplyButton>
        )}

        {simpleMenu && (
          <SimpleMenu
            className="simple-menu"
            buttonIcon={<Icon iconName="menu" />}
            {...simpleMenu}
          />
        )}
      </Card>
    </Container>
  );
}

ChatMessageCell.propTypes = propTypes;

ChatMessageCell.defaultProps = defaultProps;

export { ChatMessageCell };
