import React, { useContext, createContext } from "react";
import PropTypes from "prop-types";
import { useSocket } from "~/modules/signed/hooks/webSocket";

//* CUSTOM IMPORTS
const UserChatSocketContext = createContext(null);

const UserChatSocketProvider = ({ children }) => {
  //* REFs
  const socketRef = React.useRef(null);
  const { subscribe } = useSocket();

  //* STATES

  const [newChatMessage, setNewChatMessage] = React.useState("");
  const [readMessages, setReadMessages] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    socketRef.current = subscribe("chat:all");

    if (socketRef.current) {
      socketRef.current.on("new_message", message => {
        setNewChatMessage(message);
      });

      socketRef.current.on("read_messages", data => {
        setReadMessages(data);
      });
    }
  }, [subscribe]);

  /** This function will send the message for an chat based on chatId.
   * repliedMessageId will reference an message that will be replied.
   */
  const emitSendMessage = React.useCallback(
    (message, chatId, repliedMessageId, isNewChat) => {
      if (socketRef.current)
        socketRef.current.emit("sendMessage", {
          message,
          chat_id: chatId,
          replied_message_id: repliedMessageId,
          is_new_chat: isNewChat,
        });
    },
    []
  );

  /** This function will emit that user read the messages insite the Chat. */
  const emitReadChatMessage = React.useCallback(chatId => {
    if (socketRef.current)
      socketRef.current.emit("readChatMessage", {
        chat_id: chatId,
      });
  }, []);

  return (
    <UserChatSocketContext.Provider
      value={{
        newChatMessage,
        readMessages,
        emitSendMessage,
        emitReadChatMessage,
      }}
    >
      {children}
    </UserChatSocketContext.Provider>
  );
};

UserChatSocketProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useChatSocket = () => {
  const context = useContext(UserChatSocketContext);
  if (!context)
    throw new Error("useChatSocket must be used within UserChatSocketProvider");

  return context;
};

export { UserChatSocketProvider, useChatSocket };
