import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS
import { SearchBar } from "../../styles";
import { Container, BookmarkButtonContent } from "./styles";
import { Icon } from "~/shared/components/atoms";
import {
  CellList,
  Pagination,
  Empty,
  Infos,
} from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";
import CheckListCellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";

function Speakers({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkSpeakers,
    isSearching,
    speakersCurrentPage,
    searchString,
    paginateBookmarks,
    listBookmarks,
    setBookmarkSpeakers,
    setSearchString,
  } = useBookmarkProfile();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (!bookmarkSpeakers.isLoaded && isFetcheable)
      listBookmarks(bookmarkSpeakers.page, "speakers", setBookmarkSpeakers);
  }, [
    bookmarkSpeakers.isLoaded,
    bookmarkSpeakers.page,
    isFetcheable,
    listBookmarks,
    setBookmarkSpeakers,
  ]);

  // FUNCTIONS
  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="speakers" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="speakers" />}
      />
    );
  };

  const renderCell = speaker => {
    const { picture, name, role } = speaker;

    return (
      <Container>
        <Infos
          style={{ padding: "16px" }}
          avatar={{ src: picture, size: 68 }}
          title={name}
          subTitle={role}
          titleStyles="bold subTitle"
          subTitleStyles="opaque small"
        />
        <BookmarkButtonContent className="speaker-bookmark-button">
          <BookmarkButton
            isSelected={speaker.bookmark}
            apiType="speakers"
            apiId={speaker.id}
            variant="icon"
          />
        </BookmarkButtonContent>
      </Container>
    );
  };
  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);

          listBookmarks(
            bookmarkSpeakers.page,
            "speakers",
            setBookmarkSpeakers,
            value
          );
        }}
      />

      <CellList
        isLoaded={isUserLoaded && !bookmarkSpeakers.isPaging && !isSearching}
        renderCellLoader={() => (
          <CheckListCellLoader
            avatarSize={68}
            infosX={90}
            height={68}
            style={{ marginRight: "160px", marginBottom: "32px" }}
          />
        )}
        loaderListSize={3}
        gap={20}
        data={bookmarkSpeakers.data}
        renderCell={room => renderCell(room)}
        emptyComponent={handleEmptyMessage()}
      />

      <Pagination
        currentPage={speakersCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.NETWORK, page);
        }}
        lastPage={bookmarkSpeakers.lastPage}
      />
    </div>
  );
}

Speakers.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { Speakers };
