import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  CellList,
  cellListPropTypes,
  Empty,
  NotificationCardPropTypes,
} from "../../molecules";
import { useTranslation } from "../../../hooks/translation";
import { Spinner, Line, Typography, Icon } from "../../atoms";
import { Header, ColumnContainer, NotificationCards } from "./styles";
import { NOTIFICATION_TYPES } from "../../../utils/types";
// import { clientPaths } from "../../../routes/paths";
// import { pushState } from "../../../services/history";

const notificationListPropTypes = {
  /** Is loaded */
  isLoaded: PropTypes.bool,

  /** Array with the objetcs to be render in the list
   * {descrition(string), icon(string), isReaded(bool), time(string), title(string)}
   */
  data: NotificationCardPropTypes,

  /** Callback that is triggered when user clicks into <b>Mark all as read</b> */
  onClick: PropTypes.func,

  /** Set the visibility of the component */
  show: PropTypes.bool,

  /** Set the visibility of the component */
  isFading: PropTypes.bool,

  onBottomReached: cellListPropTypes.onBottomReached,

  isPaging: cellListPropTypes.isPaging,

  /** Callback that is triggered when user clicks to delete an notification,
   * return the clicked notification data.
   *
   * Example: (notification) => {}
   */
  onDelete: PropTypes.func,

  /** Callback that is triggered when user clicks into <b>ReadAll</b> button. */
  onReadAll: PropTypes.func,

  /** Callback that is triggered when user clicks into link in an notification,
   * return the clicked notification data.
   *
   * Example: (notification) => {}
   */
  onLinkClick: PropTypes.func,

  /** Callback that is triggered when user clicks to read in an notification,
   * return the clicked notification data.
   *
   * Example: (notification) => {}
   */
};

const notificationListDefaultProps = {
  isLoaded: false,
  data: [],
  onClick: () => {},
  show: true,
  isFading: false,
  onBottomReached: () => {},
  isPaging: false,
  onDelete: () => {},
  onReadAll: () => {},
  onLinkClick: () => {},
};

/** List of Notifications  */
function NotificationList({
  isLoaded,
  data,
  onBottomReached,
  isPaging,
  onDelete,
  onLinkClick,
  ...rest
}) {
  const { translate } = useTranslation();

  const renderCell = (cell, index) => {
    switch (cell.type) {
      case NOTIFICATION_TYPES.CONNECTION:
        return (
          <>
            <NotificationCards
              key={cell.id}
              description={cell.description}
              icon="userAdd"
              isReaded={cell.isReaded}
              time={cell.time}
              title={cell.title}
              isLoaded={cell.isLoaded}
              hasLink
              onDelete={() => onDelete(cell)}
              onLinkClick={() => onLinkClick(cell)}
              unreadLink={translate("common.clickToAccess")}
            />

            {index < data.length - 1 && <Line />}
          </>
        );
      default:
        return (
          <>
            <NotificationCards
              key={cell.id}
              description={cell.description}
              icon={cell.icon}
              isReaded={cell.isReaded}
              time={cell.time}
              title={cell.title}
              isLoaded={cell.isLoaded}
              hasLink={cell.hasLink}
              onDelete={() => onDelete(cell)}
              onLinkClick={() => onLinkClick(cell)}
              unreadLink={translate("common.clickToAccess")}
            />

            {index < data.length - 1 && <Line />}
          </>
        );
    }
  };
  return (
    <ColumnContainer {...rest}>
      <Header>
        <Typography className="small">
          {translate("components.notificationList.notifications")}
        </Typography>
      </Header>

      <CellList
        noScrollReposition
        withScroll
        orientation="column"
        data={data}
        renderCell={renderCell}
        isLoaded={isLoaded}
        onBottomReached={onBottomReached}
        isPaging={isPaging}
        loaderListSize={1}
        scrollStyle={{ maxHeight: "calc(100% - 50px)", padding: "0 16px" }}
        renderCellLoader={() => (
          <Spinner centered style={{ margin: "16px 0" }} />
        )}
        emptyComponent={
          <Empty
            style={{ marginTop: 60 }}
            icon={<Icon iconName="notifications" />}
            message={translate("components.notificationList.empty.message")}
            description={translate(
              "components.notificationList.empty.description"
            )}
          />
        }
        style={{ height: 200 }}
      />
    </ColumnContainer>
  );
}

NotificationList.propTypes = notificationListPropTypes;
NotificationList.defaultProps = notificationListDefaultProps;

export {
  NotificationList,
  notificationListDefaultProps,
  notificationListPropTypes,
};
