import styled from "styled-components";
import Scroll from "react-perfect-scrollbar";

//* CUSTOM IMPORTS
import ScheduleDetailsC from "./components/ScheduleDetails";
import ScheduleListC from "./components/ScheduleList";
import { fadeIn } from "~/shared/styles/animations";
import { medias } from "~/shared/styles";

export const Container = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 16px;

  .maxWidth {
    max-width: 450px;
  }

  .min-width {
    min-width: 450px;
  }

  @media (max-width: ${medias.wide}) {
    .min-width {
      min-width: 0;
    }
  }
`;

export const Content = styled.div`
  display: flex;

  width: 100%;

  max-width: ${props => (props.isScheduleSelected ? "900px" : "482px")};
  transition: max-width 0.4s ease-out;

  margin: 0 auto;

  .static-tabs {
    width: 100%;
    max-width: ${props => (props.isScheduleSelected ? "450px" : "482px")};

    .MuiTabs-flexContainer {
      margin-left: 16px;
    }
  }

  @media (max-width: ${medias.wide}) {
    max-width: 450px;

    .static-tabs {
      .MuiTabs-flexContainer {
        margin-left: 0px;
      }
    }
  }
`;

export const ScheduleList = styled(ScheduleListC)`
  margin: 0 auto;
  max-height: calc(100vh - 315px);
  flex-wrap: nowrap;

  .schedule-cell-container {
    max-width: 482px;
  }

  @media (max-width: ${medias.wide}) {
    .schedule-cell-container {
      max-width: 450px;
    }

    max-height: unset;
  }
`;

export const ScheduleDetailsScroll = styled(Scroll)`
  margin-top: 90px;
  max-height: calc(100vh - 315px);
  border-left: 1px solid ${props => props.theme.colors.shadow};
  margin-left: 20px;
  padding: 0 20px 20px 20px;
  overflow-x: hidden;

  @media (max-width: ${medias.wide}) {
    display: none;
  }
`;

export const ScheduleDetails = styled(ScheduleDetailsC)`
  height: fit-content;
  animation: ${fadeIn} 0.5s;
`;

export const ScheduleDetailsMobile = styled(ScheduleDetailsC)`
  display: none;
  animation: ${fadeIn} 0.5s;
  margin-top: 16px;
  margin-bottom: 8px;

  .star {
    margin-right: 20px;

    svg {
      height: 30px;
      width: 30px;
    }
  }

  @media (max-width: ${medias.wide}) {
    display: block;
  }
`;
