import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { Container, Wallpaper, Avatar, Button } from "./styles";
import PrivacySettingModal from "../../../../components/modals/PrivacySettingModal";
import DisconnectModal from "../../../../components/modals/DisconnectModal";
import { Icon, Typography } from "~/shared/components/atoms";
import { FooterButtons, SimpleMenu } from "~/shared/components/molecules";
import { ProfileEditModal } from "../../../../components/modals/ProfileEditModal";
import { useTranslation } from "~/shared/hooks/translation";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { CONNECTION_TYPES } from "~/shared/utils/types";
import { useUserChat } from "~/modules/signed/modules/client/hooks/chat/index";
import { useProfileGamification } from "../../hooks/gamification";
import { useGamification } from "~/modules/signed/hooks/gamification";

const profileHeaderPropTypes = {
  user: PropTypes.object.isRequired,
  itsMe: PropTypes.bool.isRequired,
};

const profileHeaderDefaultPropTypes = {};

const ProfileHeader = ({ user, itsMe }) => {
  //* STATES
  const {
    createConnection,
    selectedUser,
    changeSettings,
    buttonStatus,
    setButtonStatus,
    contactState,
    chatState,
    setContactState,
    setChatState,
    isSettingsLoaded,
    isUserLoaded,
  } = useProfile();
  const { openChat } = useUserChat();
  const { avatar, name, id } = selectedUser;
  const [showPrivacyModal, setShowPrivacyModal] = React.useState(false);
  const [showEditModal, setShowEditModal] = React.useState(false);
  const [showDisconnectModal, setShowDisconnectModal] = React.useState(false);
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);
  const { profilePoints } = useProfileGamification();
  const { renderGamificationFrame, userPoints } = useGamification();

  //* FUNCTIONS
  const options = [
    {
      id: 1,
      description: translate("entities.profile.header.allowContacts"),
      isSelected: contactState,
      onChange: () => {
        setContactState(!contactState);
      },
    },
    {
      id: 2,
      description: translate("entities.profile.header.allowChat"),
      isSelected: chatState,
      onChange: () => {
        setChatState(!chatState);
      },
    },
    // {
    //   id: 3,
    //   description: translate("entities.profile.header.showStats"),
    //   isSelected: statusState,
    //   onChange: () => {
    //     setStatusState(!statusState);
    //   },
    // },
  ];
  const { updateConnection } = useProfile();

  const fillItems = () => {
    const items = [
      {
        id: 1,
        name: translate("entities.profile.header.disconnect"),
        icon: <Icon iconName="userRemove" variant="accent" />,
        function: () => {
          setShowDisconnectModal(true);
        },
        color: colors.redWrong,
      },
    ];

    return items;
  };

  const handleOnUserClick = React.useCallback(
    () => openChat(name, avatar, [id], id),
    [avatar, id, name, openChat]
  );

  const handleShowPrivacyModal = () => {
    setShowPrivacyModal(true);
  };
  const handleShowEditModal = () => {
    setShowEditModal(true);
  };

  const handleSaveSettings = () => {
    changeSettings(user.id, {
      contact: contactState,
      chat: chatState,
    });

    setShowPrivacyModal(false);
  };

  const handleRenderButton = () => {
    if (isUserLoaded) {
      if (contactState) {
        switch (buttonStatus.status) {
          case CONNECTION_TYPES.CONNECTED:
            return (
              <>
                <Button
                  icon={<Icon iconName="addedUser" variant="accent" />}
                  variant="outlined"
                  tip={translate("entities.profile.header.connectedMessage")}
                  tipSize="200"
                >
                  <Typography className="bold">
                    {translate("entities.profile.header.connected")}
                  </Typography>
                </Button>
                <SimpleMenu
                  buttonIcon={
                    <Icon iconName="menu" variant="accent" size={35} />
                  }
                  fillItems={fillItems}
                />
              </>
            );

          case CONNECTION_TYPES.PENDING:
            if (buttonStatus.type === CONNECTION_TYPES.SOURCE) {
              return (
                <>
                  <Button
                    icon={<Icon iconName="userAdd" variant="white" />}
                    variant="accent"
                    onClick={() => {
                      setButtonStatus({ status: CONNECTION_TYPES.CONNECTED });
                      updateConnection(
                        id,
                        CONNECTION_TYPES.CONNECTED,
                        CONNECTION_TYPES.PENDING
                      );
                    }}
                    tip={translate("entities.profile.header.requestAccept")}
                    tipSize="160"
                  >
                    {translate("entities.profile.header.accept")}
                  </Button>
                  <Button
                    icon={<Icon iconName="userRemove" variant="accent" />}
                    variant="outlined"
                    onClick={() => {
                      setButtonStatus({
                        status: CONNECTION_TYPES.DISCONNECTED,
                      });
                      updateConnection(
                        id,
                        CONNECTION_TYPES.DISCONNECTED,
                        CONNECTION_TYPES.PENDING
                      );
                    }}
                    tip={translate("entities.profile.header.requestRefuse")}
                    tipSize="160"
                  >
                    {translate("entities.profile.header.refuse")}
                  </Button>
                </>
              );
            }
            return (
              <Button
                icon={<Icon iconName="userRemove" variant="accent" />}
                variant="outlined"
                onClick={() => {
                  setButtonStatus({ status: CONNECTION_TYPES.DISCONNECTED });
                  updateConnection(
                    id,
                    CONNECTION_TYPES.DISCONNECTED,
                    CONNECTION_TYPES.PENDING
                  );
                }}
                tip={translate("entities.profile.header.requestCancel")}
                tipSize="200"
              >
                {translate("entities.profile.header.cancel")}
              </Button>
            );

          default:
            return (
              <Button
                icon={<Icon iconName="userAdd" variant="white" />}
                onClick={() => {
                  setButtonStatus({
                    status: CONNECTION_TYPES.PENDING,
                    type: CONNECTION_TYPES.TARGET,
                  });
                  createConnection();
                }}
                tip={translate("entities.profile.header.requestSend")}
                tipSize="200"
              >
                <Typography className="bold">
                  {translate("entities.profile.header.connect")}
                </Typography>
              </Button>
            );
        }
      }
      return <></>;
    }
  };

  const gamificationPosition = React.useMemo(() => {
    if (itsMe) if (userPoints) return userPoints.rank_position;

    if (profilePoints) return profilePoints.rank_position;

    return null;
  }, [itsMe, profilePoints, userPoints]);

  const gamificationPoints = React.useMemo(() => {
    if (itsMe) if (userPoints) return userPoints.points;

    if (profilePoints) return profilePoints.points;

    return null;
  }, [itsMe, profilePoints, userPoints]);

  return (
    <Container id="profile-header">
      <Wallpaper>
        <Avatar
          avatarBadge="none"
          avatar={avatar}
          frame={renderGamificationFrame(gamificationPosition)}
          avatarSize={140}
          label={name}
          subTitle={gamificationPoints ? `${gamificationPoints}pts` : null}
        />
      </Wallpaper>

      <footer id="profile-footer-header">
        {itsMe ? (
          <>
            <Button
              variant="outlined"
              icon={<Icon iconName="gear" variant="accent" />}
              tip={translate("entities.profile.header.privacySettings")}
              tipSize={120}
              onClick={handleShowPrivacyModal}
            />
            <Button onClick={handleShowEditModal}>
              {translate("pages.editProfile.title")}
            </Button>
          </>
        ) : (
          <>
            {isSettingsLoaded ? (
              <>
                {chatState ? (
                  <Button
                    variant="outlined"
                    icon={<Icon iconName="chat" variant="accent" />}
                    tip={translate("entities.profile.header.sendMessage")}
                    tipSize={120}
                    onClick={handleOnUserClick}
                  />
                ) : (
                  <div style={{ margin: "24px" }}> </div>
                )}

                {handleRenderButton()}
              </>
            ) : (
              <div style={{ marginBottom: "54px" }} />
            )}
          </>
        )}
      </footer>

      <PrivacySettingModal
        user={user}
        show={showPrivacyModal}
        onClose={() => setShowPrivacyModal(false)}
        footerComponent={
          <FooterButtons
            onClick={handleSaveSettings}
            onCancel={() => setShowPrivacyModal(false)}
            style={{ margin: 16 }}
          />
        }
        title={translate("entities.profile.header.privacySettings")}
        options={options}
      />

      <DisconnectModal
        user={user}
        options={null}
        show={showDisconnectModal}
        onCancel={() => setShowDisconnectModal(false)}
        onClose={() => setShowDisconnectModal(false)}
        onConfirm={() => {
          setShowDisconnectModal(true);
          setButtonStatus(CONNECTION_TYPES.DISCONNECTED);
          updateConnection(
            id,
            CONNECTION_TYPES.DISCONNECTED,
            CONNECTION_TYPES.CONNECTED
          );
          setShowDisconnectModal(false);
        }}
      />

      <ProfileEditModal
        show={showEditModal}
        onClose={() => setShowEditModal(false)}
      />
    </Container>
  );
};

ProfileHeader.propTypes = profileHeaderPropTypes;
ProfileHeader.defaultProps = profileHeaderDefaultPropTypes;

export { ProfileHeader, profileHeaderPropTypes, profileHeaderDefaultPropTypes };
