import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//*  CUSTOM IMPORTS
import { medias } from "../../styles";

const WindowContext = createContext(null);

const WindowProvider = ({ children }) => {
  //* STATES
  const [windowWidth, setWindowWidth] = React.useState(
    document.body.clientWidth
  );

  //* FUNCTIONS
  window.onresize = () => {
    setWindowWidth(document.body.clientWidth);
  };

  const isMobile = windowWidth <= medias.MOBILE;

  const isTablet = windowWidth <= medias.TABLET && windowWidth > medias.MOBILE;

  const isWide = windowWidth <= medias.WIDE && windowWidth > medias.TABLET;

  const isMediaLargeTablet = windowWidth <= medias.LARGE_TABLET;

  const isBelowWide = windowWidth <= medias.WIDE;

  return (
    <WindowContext.Provider
      value={{
        windowWidth,
        isMobile,
        isTablet,
        isWide,
        isMediaLargeTablet,
        isBelowWide,
      }}
    >
      {children}
    </WindowContext.Provider>
  );
};

WindowProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

const useWindow = () => {
  const context = useContext(WindowContext);

  if (!context) throw new Error("useWindow must be used within WindowProvider");

  return context;
};

export { WindowProvider, useWindow };
