import React from "react";
import Icon from "@mdi/react";
import { mdiEmail } from "@mdi/js";
import { MdArrowBack } from "react-icons/md";
import { useSelector, useDispatch } from "react-redux";
import { Form } from "@unform/web";
import * as Yup from "yup";

//* ACTIONS
import { recoverPassword } from "~/modules/auth/store/auth/actions";

//* CUSTOM IMPOSTS
import { useTranslation } from "~/shared/hooks/translation";
import logo from "~/shared/assets/custom/logo@2x.png";
import { Button, Input, Trans } from "~/shared/components/atoms";
import { isDataValid } from "~/shared/utils/validations";
import { getYupErrors } from "~/shared/utils/forms";
import { openSignIn } from "~/shared/routes/navigation";

function ForgotPassword() {
  //* REDUCER
  const dispatch = useDispatch();
  const { isRecovering } = useSelector(state => state.auth);

  //* REFS
  const formRef = React.useRef(null);

  //* FUNCTIONS
  const { translate } = useTranslation();

  const handleSubmit = React.useCallback(
    async data => {
      function dispatchRecoverPassword() {
        dispatch(recoverPassword({ email: data.email }));
      }

      try {
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const schema = Yup.object().shape({
          email: Yup.string()
            .email(translate("handlers.email.isEmailInvalid"))
            .trim()
            .required(translate("handlers.email.isRequired")),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        dispatchRecoverPassword();
      } catch (err) {
        const errors = getYupErrors(err);

        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [dispatch, translate]
  );

  return (
    <>
      <button className="auth-back-button" type="button" onClick={openSignIn}>
        <MdArrowBack size={16} />
      </button>

      <img src={logo} alt="Logo do evento" className="auth-logo" />

      <Form ref={formRef} onSubmit={handleSubmit}>
        <h3>
          <Trans translationKey="pages.forgotPassword.title">
            Recuperar senha
          </Trans>
        </h3>

        <p className="auth-paragraph">
          <Trans translationKey="pages.forgotPassword.subTitle">
            Insira o email que usou para criar uma conta nesse app e te
            enviaremos um e-mail com instruções de como recuperar sua senha.
          </Trans>
        </p>

        <Input
          title={translate("components.input.email.title")}
          type="email"
          name="email"
          className="auth-input"
          placeholder={translate("components.input.email.placeholder")}
          icon={<Icon path={mdiEmail} />}
        />

        <Button className="auth-button" type="submit" loading={isRecovering}>
          <Trans translationKey="pages.forgotPassword.submitButton">
            Recuperar
          </Trans>
        </Button>
      </Form>
    </>
  );
}

export default ForgotPassword;
