import types from "./types";

// REF TYPES
// This refs will control where the extra links has to be craeted
export const REF_TYPES = {
  BROADCAST_EXTRA_PLAYER: "broadcastroom_extraplayer",
  BROADCAST_EXTRA_BUTTON: "broadcastroom_extrabutton",
  BROADCAST_EXTRA_EMBED: "broadcastroom_extraembed",
  PARTNER_EXTRA_BUTTON: "partner_extrabutton",
};

// EXTRA EMBEDS
export function clearExtraEmbed() {
  return {
    type: types.EXTRA_EMBED.CLEAR_EMBED,
  };
}

// LIST
export function listExtraEmbeds(refId) {
  return {
    type: types.EXTRA_EMBED.LIST.REQUEST,
    payload: { refId },
  };
}
export function listExtraEmbedsSuccessful({ extraEmbeds }) {
  return {
    type: types.EXTRA_EMBED.LIST.SUCCESSFUL,
    payload: { extraEmbeds },
  };
}
export function listExtraEmbedsFailed() {
  return {
    type: types.EXTRA_EMBED.LIST.FAILED,
  };
}

// CREATE
export function createExtraEmbed(extraEmbed) {
  return {
    type: types.EXTRA_EMBED.CREATE.REQUEST,
    payload: { extraEmbed },
  };
}
export function createExtraEmbedSuccessful(extraEmbed) {
  return {
    type: types.EXTRA_EMBED.CREATE.SUCCESSFUL,
    payload: { extraEmbed },
  };
}
export function createExtraEmbedFailed() {
  return {
    type: types.EXTRA_EMBED.CREATE.FAILED,
  };
}

// UPDATE
export function updateExtraEmbed(extraEmbed, extraEmbedId) {
  return {
    type: types.EXTRA_EMBED.UPDATE.REQUEST,
    payload: { extraEmbed, extraEmbedId },
  };
}
export function updateExtraEmbedSuccessful(extraEmbed, extraEmbedId) {
  return {
    type: types.EXTRA_EMBED.UPDATE.SUCCESSFUL,
    payload: { extraEmbed, extraEmbedId },
  };
}

export function updateExtraEmbedFailed() {
  return {
    type: types.EXTRA_EMBED.UPDATE.FAILED,
  };
}

// DELETE
export function deleteExtraEmbed(extraEmbedId) {
  return {
    type: types.EXTRA_EMBED.DELETE.REQUEST,
    payload: { extraEmbedId },
  };
}
export function deleteExtraEmbedSuccessful(extraEmbedId) {
  return {
    type: types.EXTRA_EMBED.DELETE.SUCCESSFUL,
    payload: { extraEmbedId },
  };
}
export function deleteExtraEmbedFailed() {
  return {
    type: types.EXTRA_EMBED.DELETE.FAILED,
  };
}

// CLEAR
export function clearExtraEmbeds() {
  return {
    type: types.EXTRA_EMBED.CLEAR_LIST,
  };
}

