import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import { MdTimeline } from "react-icons/md";

// ACTIONS
import {
  listUserActivities,
  clearUserActivities,
} from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import { isDataValid, isListValid } from "~/shared/utils/validations";
import ActivityCell from "~/modules/signed/modules/cms/components/cells/ActivityCell";
import LoaderCell from "../../../../../../../shared/components/molecules/cellLoaders/ActivityCellLoader";
import { Scroll, Container, EmptyContainer, ContentLoader } from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function ActivityList({ className, userId }) {
  const { translate } = useTranslation();

  const dispatch = useDispatch();
  const { data, isLoaded, isPaging } = useSelector(
    state => state.adminUsers.activities
  );
  const { data: activities, lastPage } = data;

  // STATES
  const [page, setPage] = React.useState(1);
  const hasMoreItens = page < lastPage;

  // FUNCTIONS
  useEffect(() => {
    if (isDataValid(userId)) dispatch(listUserActivities({ userId, page }));
  }, [dispatch, page, userId]);

  useEffect(() => {
    return () => {
      dispatch(clearUserActivities());
    };
  }, [dispatch]);

  function handleNextPage() {
    if (hasMoreItens && !isPaging) setPage(page + 1);
  }

  return isLoaded ? (
    <>
      {isListValid(activities) ? (
        <Scroll onYReachEnd={handleNextPage} className={className}>
          <Container>
            {activities.map((activity, index) => (
              <ActivityCell
                activity={activity}
                noLine={index === activities.length - 1}
              />
            ))}

            {isPaging && <ContentLoader cellLoader={<LoaderCell />} size={3} />}
          </Container>
        </Scroll>
      ) : (
        <EmptyContainer>
          <MdTimeline />
          <h4>{translate("entities.registrationFields.activity")}</h4>
        </EmptyContainer>
      )}
    </>
  ) : (
    <ContentLoader cellLoader={<LoaderCell />} size={5} />
  );
}

ActivityList.propTypes = {
  userId: PropTypes.number.isRequired,
};

export default ActivityList;
