import React, { useContext, createContext } from "react";
import { useSelector } from "react-redux";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { useSocket } from "~/modules/signed/hooks/webSocket";

const NotificationSocketContext = createContext(null);

const NotificationSocketProvider = ({ children }) => {
  const { id: userId } = useSelector(state => state.user.profile.data);
  const { subscribe, closeChannel } = useSocket();

  //* REFS
  const socketRef = React.useRef(null);

  //* STATES
  const [newNotification, setNewNotification] = React.useState(null);

  //* FUNCTIONS
  const emitNotificationReceived = React.useCallback(
    notificationId => {
      if (socketRef.current)
        socketRef.current.emit("notificationLog", {
          user_id: userId,
          notification_id: notificationId,
          action: NOTIFICATION_TYPES.RECEIVED,
        });
    },
    [userId]
  );

  const emitNotificationOpened = React.useCallback(
    notificationId => {
      if (socketRef.current)
        socketRef.current.emit("notificationLog", {
          user_id: userId,
          notification_id: notificationId,
          action: NOTIFICATION_TYPES.OPENED,
        });
    },
    [userId]
  );

  React.useEffect(() => {
    socketRef.current = subscribe("notification:all");

    if (socketRef.current) {
      socketRef.current.on("notification", notification => {
        setNewNotification(notification);

        emitNotificationReceived(notification.id);
      });
    }
  }, [emitNotificationOpened, emitNotificationReceived, subscribe, userId]);

  React.useEffect(() => {
    return () => {
      closeChannel(socketRef.current);
    };
  }, [closeChannel]);

  return (
    <NotificationSocketContext.Provider
      value={{
        newNotification,
        emitNotificationReceived,
        emitNotificationOpened,
      }}
    >
      {children}
    </NotificationSocketContext.Provider>
  );
};

NotificationSocketProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useNotificationSocket = () => {
  const context = useContext(NotificationSocketContext);
  if (!context)
    throw new Error(
      "useNotificationSocket must be used within NotificationSocketProvider"
    );

  return context;
};

export { NotificationSocketProvider, useNotificationSocket };
