import React from "react";

//* APIS
import { adminDeleteStickyNote } from "../../../../../cms/modules/admin/apis/stickyNotes";
import {
  apiCreateStickyNotes,
  apiListStickyNotes,
  apiDeleteStickyNote,
} from "../../../../apis/stickyNotes";
import { createActiveLog } from "~/modules/signed/modules/client/apis/activeLog";

//* CUSTOM IMPORTS
import { CRUDModal } from "~/shared/components/organisms/";
import { onBottomListener } from "~/shared/utils/scrolls";
import { StickyNotesFormModal, StickyNoteDetailsModal } from "./components";
import { useStickyNotes } from "./hooks";
import { API_STATUS } from "~/shared/utils/apis";
import { useTranslation } from "~/shared/hooks/translation";
import { StickyNotesList } from "./styles";
import { ACTIVE_LOG_TYPES } from "~/shared/utils/types";
import { useSigned } from "~/modules/signed/hooks";

const StickyNotes = () => {
  const {
    addStickyNote,
    stickyNotes,
    clearStickyNotes,
    receiveStickNotes,
    paginateStickNotes,
    deleteStickNote,
  } = useStickyNotes();
  const { translate } = useTranslation();
  const { isMyUserAdmin } = useSigned();

  //* STATES
  const [showForm, setShowForm] = React.useState(false);
  const [showDetails, setShowDetails] = React.useState(false);
  const [stickyNoteData, setStickyNoteData] = React.useState(null);
  const [apiStatus, setApiStatus] = React.useState(API_STATUS.IDLE);
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  //* FUNCTIONS
  const onPaginate = React.useCallback(() => {
    onBottomListener(async () => {
      if (stickyNotes.page < stickyNotes.lastPage) {
        setApiStatus(API_STATUS.PAGING);

        await apiListStickyNotes({
          page: stickyNotes.page + 1,
        }).then(response => paginateStickNotes(response.data));

        setApiStatus(API_STATUS.IDLE);
      }
    });
  }, [paginateStickNotes, stickyNotes.lastPage, stickyNotes.page]);

  React.useEffect(() => {
    window.addEventListener("scroll", onPaginate);

    return () => {
      window.removeEventListener("scroll", onPaginate);
    };
  }, [onPaginate]);

  React.useEffect(() => {
    const list = async () => {
      await apiListStickyNotes({ page: 1 }).then(response => {
        receiveStickNotes(response.data);
      });
    };
    list();
  }, [receiveStickNotes]);

  React.useEffect(() => {
    return () => {
      clearStickyNotes();
    };
  }, [clearStickyNotes]);

  const handleSubmit = async data => {
    setApiStatus(API_STATUS.CREATING);
    setShowForm(false);

    await apiCreateStickyNotes({ data }).then(response => {
      addStickyNote(response.data);
      setApiStatus(API_STATUS.IDLE);
    });
  };

  const deleteStickyNoteSuccess = id => {
    deleteStickNote(id);
    setStickyNoteData(null);
  };

  const handleDeleteStickyNote = async () => {
    const { id } = stickyNoteData;

    setApiStatus(API_STATUS.DELETING);
    setShowDeleteModal(false);

    if (isMyUserAdmin) {
      await adminDeleteStickyNote({ id }).then(() => {
        deleteStickyNoteSuccess(id);
      });
    } else {
      await apiDeleteStickyNote({ id }).then(() => {
        deleteStickyNoteSuccess(id);
      });
    }

    setApiStatus(API_STATUS.IDLE);
  };

  const handleOnStickyNoteClick = stickyNote => {
    createActiveLog({
      action: ACTIVE_LOG_TYPES.STICKY_NOTE_CLICK,
      refId: stickyNote.id,
    });
    setStickyNoteData(stickyNote);
    setShowDetails(true);
  };

  return (
    <>
      <StickyNotesList
        cellId={stickyNoteData && stickyNoteData.id}
        isCreating={apiStatus === API_STATUS.CREATING}
        isDeleting={apiStatus === API_STATUS.DELETING}
        isPaging={apiStatus === API_STATUS.PAGING}
        title={translate("entities.stickyNotes.plural")}
        data={stickyNotes.data}
        isLoaded={stickyNotes.isLoaded}
        onAdd={() => setShowForm(true)}
        onStickyNoteClick={handleOnStickyNoteClick}
        onDelete={data => {
          setStickyNoteData(data);
          setShowDeleteModal(true);
        }}
        containerStyle={{ marginBottom: 30 }}
      />

      {showForm && (
        <StickyNotesFormModal
          show
          onClose={() => setShowForm(false)}
          onSubmit={handleSubmit}
        />
      )}

      {showDetails && stickyNoteData && (
        <StickyNoteDetailsModal
          show
          onClose={() => {
            setShowDetails(false);
            setStickyNoteData(null);
          }}
          data={stickyNoteData}
        />
      )}

      {showDeleteModal && (
        <CRUDModal
          show
          onClose={() => setShowDeleteModal(false)}
          type="delete"
          entity={translate("entities.stickyNotes.singular")}
          entityName={translate("entities.stickyNotes.yourStickyNote")}
          onConfirm={handleDeleteStickyNote}
        />
      )}
    </>
  );
};

export default StickyNotes;
