import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS
import { SearchBar } from "../../styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { MarketRoomUserViewList } from "~/modules/signed/modules/client/components/lists/MarketRoomUserList";
import NetworkCellLoader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";

function VirtualFair({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkMarketRooms,
    isSearching,
    marketRoomsCurrentPage,
    searchString,
    paginateBookmarks,
    listMarket,
    setSearchString,
  } = useBookmarkProfile();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (!bookmarkMarketRooms.isLoaded && isFetcheable) listMarket();
  }, [bookmarkMarketRooms.isLoaded, isFetcheable, listMarket]);

  // FUNCTIONS
  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="marketRooms" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="marketRooms" />}
      />
    );
  };

  const renderCell = marketRoom => {
    const { name, partners, level } = marketRoom;

    return (
      <MarketRoomUserViewList
        name={name}
        partners={partners}
        level={level}
        isLastOne={false}
        search={searchString}
        isLoaded
      />
    );
  };
  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);

          listMarket(value);
        }}
      />

      <CellList
        isLoaded={isUserLoaded && !isSearching}
        cellStyle={{ width: "100%" }}
        renderCellLoader={() => <NetworkCellLoader />}
        loaderListSize={3}
        gap={20}
        data={bookmarkMarketRooms.data}
        renderCell={marketRoom => renderCell(marketRoom)}
        emptyComponent={handleEmptyMessage()}
      />

      <Pagination
        currentPage={marketRoomsCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.ROOMS, page);
        }}
        lastPage={bookmarkMarketRooms.lastPage}
      />
    </div>
  );
}

VirtualFair.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { VirtualFair };
