import React from "react";
import { useLocation } from "react-router-dom";

//* APIs
import {
  getBroadcastRoomsReportCount,
  getBroadcastRoomsReportCountAccess,
} from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";
import { getAccessByBroadcastRoomExport } from "~/modules/signed/modules/cms/modules/admin/apis/exports/broadcastRooms";

//* CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminDashboard } from "~/shared/routes/navigation";
import { AccessDetailsTemplate } from "~/shared/components/templates";
import { Stars } from "~/shared/components/molecules";
import { Icon, Typography } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function BroadcastRoomsAccess() {
  const location = useLocation();
  const { setBreadCrumbs } = useCMS();

  //* STATES
  const getTimezone = location.state.timezone;
  const [dateInterval, setDateInterval] = React.useState(() => {
    if (isDataValid(location.state))
      return {
        startDate: location.state.startDate,
        endDate: location.state.endDate,
      };

    return {
      startDate: null,
      endDate: null,
    };
  });
  const [reportCount, setReportCount] = React.useState({
    isLoaded: false,
    data: null,
  });
  const [reportCountAccess, setReportCountAccess] = React.useState({
    isLoaded: false,
    data: null,
  });
  const [search, setSearch] = React.useState("");
  const [pagination, setPagination] = React.useState({
    page: 1,
    lastPage: 1,
  });

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("common.dashboard"),
        function: openAdminDashboard,
      },
      {
        id: 2,
        name: translate("entities.broadcastRooms.accessesPerRoom"),
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, translate]);

  React.useEffect(() => {
    const { endDate, startDate } = dateInterval;

    const fetch = async () => {
      setReportCount({ isLoaded: false, data: [] });

      await getBroadcastRoomsReportCount({ endDate, startDate }).then(
        response => {
          setReportCount({ isLoaded: true, data: response.data });
        }
      );
    };

    fetch();
  }, [dateInterval]);

  const fetchReportCountAccess = React.useCallback(
    page => {
      const { endDate, startDate } = dateInterval;

      const fetch = async () => {
        setReportCountAccess({ isLoaded: false, data: [] });

        await getBroadcastRoomsReportCountAccess({
          endDate,
          startDate,
          perPage: 7,
          page,
          search,
        }).then(response => {
          const { data, page: responsePage, lastPage } = response.data;

          setReportCountAccess({ isLoaded: true, data });
          setPagination({ page: responsePage, lastPage });
        });
      };

      fetch();
    },
    [dateInterval, search]
  );

  React.useEffect(() => {
    fetchReportCountAccess(1);
  }, [fetchReportCountAccess]);

  const renderTableRows = () => {
    const { data } = reportCountAccess;

    return data.map(row => {
      if (!row) return null;

      const {
        title,
        access_count: access,
        unique_access_count: leads,
        rate_avg: rate,
      } = row;

      return {
        title: (
          <Typography
            className="accent subTitle bold"
            maxLines={1}
            style={{ marginRight: 10 }}
          >
            {dTranslate(title)}
          </Typography>
        ),
        access: <Typography className="subTitle">{access}</Typography>,
        leads: <Typography className="subTitle">{leads}</Typography>,
        evaluation: <Stars isLoaded initialRate={rate} readOnly />,
      };
    });
  };

  return (
    <AccessDetailsTemplate
      onSearch={value => setSearch(value)}
      searchBarPlaceholder={translate("entities.broadcastRooms.search")}
      dateInterval={dateInterval}
      onDateSelect={date => setDateInterval(date)}
      downloadButtonData={{
        placeholder: translate("entities.reports.downloadReportInXlsx"),
        callDownloadSheetsApi: () =>
          getAccessByBroadcastRoomExport({
            endDate: dateInterval.endDate,
            startDate: dateInterval.startDate,
            timezone: getTimezone,
          }),
        sheetsName: translate("entities.reports.broadcastRooms"),
      }}
      cardData={{
        title: translate("entities.broadcastRooms.plural"),
        dotColor: "#2BFD6A",
        isLoaded: reportCount.isLoaded,
        data: [
          {
            id: 1,
            counter:
              reportCount.isLoaded && reportCount.data.total_unique_user_access,
            description: translate("entities.broadcastRooms.participants"),
          },
          {
            id: 2,
            counter:
              reportCount.isLoaded &&
              String(reportCount.data.rate)
                .substring(0, 3)
                .replace(".", ","),
            description: translate(
              "entities.broadcastRooms.averageRoomEvaluation"
            ),
          },
          {
            id: 3,
            counter:
              reportCount.isLoaded && reportCount.data.count_broadcast_room,
            description: translate("entities.broadcastRooms.rooms"),
          },
          {
            id: 4,
            counter:
              reportCount.isLoaded && reportCount.data.total_unique_access,
            description: translate("common.accesses"),
          },
        ],
      }}
      tableData={{
        columnSizes: ["45%", "15%", "15%", "25%"],
        isLoading: !reportCountAccess.isLoaded,
        rows: reportCountAccess.isLoaded && renderTableRows(),
        titles: [
          translate("entities.broadcastRooms.plural"),
          translate("common.accesses"),
          translate("entities.broadcastRooms.participants"),
          translate("common.evaluation"),
        ],
        empty: {
          search,
          variant: search && "search",
          message: translate("entities.broadcastRooms.emptyAccesses.message"),
          description: translate(
            "entities.broadcastRooms.emptyAccesses.description"
          ),
          icon: <Icon iconName="broadcastRooms" />,
        },
      }}
      tablePagination={{
        currentPage: pagination.page,
        lastPage: pagination.lastPage,
        handlePagination: page => fetchReportCountAccess(page),
      }}
    />
  );
}

export default BroadcastRoomsAccess;
