export const Wrapper = styled.div`
  height: 100%;
`;

export const Background = styled.img`
  position: fixed;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  z-index: -1;
  object-fit: cover;
`;

export const Splash = styled.div`
  height: 552px;
  width: 308px;
  background: url(${props => props.src}) center;
  background-size: cover;
  border-radius: 0 8px 8px 0;
`;

