import React from "react";
import PropTypes from "prop-types";
import { MdStar } from "react-icons/md";

//* APIs
import { getBroadcastRoomsExportExtract } from "~/modules/signed/modules/cms/modules/admin/apis/exports/broadcastRooms";
import { getBroadcastRoomsReportCount } from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { DashboardReportCard } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function BroadcastRoomsCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: broadcastRoomsReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getBroadcastRoomsReportCount({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardReportCard
      isLoaded={isLoaded}
      title={translate("entities.broadcastRooms.plural")}
      downloadButtonData={{
        callDownloadSheetsApi: () =>
          getBroadcastRoomsExportExtract({
            endDate: dateInterval.endDate,
            startDate: dateInterval.startDate,
            timezone,
          }),
        sheetsName: translate("entities.reports.broadcastRooms"),
      }}
      dotColor="#2BFD6A"
      reportItems={[
        {
          id: 1,
          counter: isLoaded && broadcastRoomsReport.total_unique_user_access,
          description: translate("entities.broadcastRooms.participants"),
        },
        {
          id: 2,
          counter:
            isLoaded && broadcastRoomsReport.rate
              ? String(broadcastRoomsReport.rate)
                  .substring(0, 3)
                  .replace(".", ",")
              : 0,
          description: translate(
            "entities.broadcastRooms.averageRoomEvaluation"
          ),
          icon: <MdStar />,
        },
      ]}
    />
  );
}

BroadcastRoomsCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
  timezone: PropTypes.number.isRequired,
};

export default BroadcastRoomsCard;
