import styled, { css } from "styled-components";
import PerfectScroll from "react-perfect-scrollbar";
import { medias } from "~/shared/styles";

//* CUSTOM IMPORTS
import { FooterButtons as FooterButtonsC } from "../../molecules";

export const Wrapper = styled.div`
  display: flex;
  height: 100%;
  width: 100%;
  background: #fff;
`;

export const ScrollCard = styled(PerfectScroll)`
  padding: 40px 4% 0px 4%;
  margin: 10px 10px 10px 0;
  display: flex;
  flex-direction: column;
  flex: 1;
  max-height: calc(100vh - 20px);
  border-radius: 16px;
  background: ${props => props.theme.colors.background};
  transition: max-height 0.3s ease-in;

  ${props =>
    props.editMode &&
    css`
      max-height: calc(100vh - 70px);
    `}
`;

export const Title = styled.h2`
  width: 100%;
  text-align: left;
  font-size: 24px;
  font-weight: bold;
  color: ${props => props.theme.colors.accent};
`;

export const Children = styled.div`
  flex: 1;
  min-width: ${medias.tablet};
`;

export const FooterButtons = styled(FooterButtonsC)`
  visibility: ${props => (props.isVisible ? "visible" : "hidden")};
  opacity: ${props => (props.isVisible ? 1 : 0)};
  transition: opacity 1s ease-in;

  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10px 30px;
`;
