import { pushState } from "~/shared/services/history";
import { clientPaths } from "./paths";

export const openStickyNotes = () =>
  pushState(clientPaths.home.stickyNotes.index);

export const openHome = () => pushState(clientPaths.home.broadcastRooms.index);

export const openDownloadMaterials = () =>
  pushState(clientPaths.home.donwloadMaterials.index);

export const openBroadcastRooms = () =>
  pushState(clientPaths.home.broadcastRooms.index);

export const openMarketRooms = () =>
  pushState(clientPaths.home.marketRooms.index);

export const openNetworkRooms = () =>
  pushState(clientPaths.home.networkRooms.index);

export const openPosters = () => pushState(clientPaths.home.posters.index);

export const openMyCertificates = () =>
  pushState(clientPaths.home.certificates.index);

export const openBroadcastRoom = ({ broadcastRoomId, title }) =>
  pushState(`${clientPaths.broadcastRooms.index}/${broadcastRoomId}`, {
    code: broadcastRoomId,
    title,
  });

export const openFolderDetailsMaterials = folderId => {
  pushState(`${clientPaths.home.donwloadMaterials.index}/${folderId}`);
};

export const openMarketRoom = (partnerId, partnerTitle) => {
  pushState(`${clientPaths.marketRooms.index}/${partnerId}`, { partnerTitle });
};

export const openRanking = () => {
  pushState(`${clientPaths.home.ranking.index}`);
};

export const openQuestions = roomId => {
  window.open(`${clientPaths.broadcastRooms.questions}/${roomId}`);
};

export const openProfile = () => {
  pushState(clientPaths.profile.index);
};

export const openUserProfile = userId => {
  pushState(`${clientPaths.profile.index}/${userId}`);
};

export const openPosterDetails = posterId => {
  pushState(`${clientPaths.posters.index}/${posterId}`);
};

export const openMySchedule = (meetingId = null) => {
  pushState(
    `${clientPaths.mySchedule.index}${
      meetingId ? `?meetingId=${meetingId}` : ""
    }`
  );
};
