import React from "react";
import PropTypes from "prop-types";

//* APIs
import { uploadFile } from "../../apis/files";

//* CUSTOM IMPORTS
import { FileInput } from "../../../../../../shared/components/organisms";

const adminFileInputPropTypes = {
  /** The initial value for the <b>FileInput</b> */
  initialValue: PropTypes.string,

  /** Callback that is triggered after user upload file into server,
   * return the URL of the uploaded file.
   *
   * Example: (url) => {}
   */
  onUploadFinish: PropTypes.func,
};

const adminFileInputDefaultProps = {
  initialValue: null,
  onUploadFinish: () => {},
};

/** This is an HOC component, so it only implements an component
 * with an API call
 */
const AdminFileInput = ({ initialValue, onUploadFinish, ...rest }) => {
  //* STATES
  const [src, setSrc] = React.useState(initialValue);
  const [isUploading, setIsUploading] = React.useState(false);

  //* FUNCTIONS
  async function handleOnUpload(file) {
    setIsUploading(true);

    await uploadFile({ file }).then(response => {
      setSrc(response.data.url);
      onUploadFinish(response.data.url);
    });

    setIsUploading(false);
  }

  return (
    <FileInput
      {...rest}
      src={src}
      isUploading={isUploading}
      onUpload={handleOnUpload}
    />
  );
};

AdminFileInput.propTypes = adminFileInputPropTypes;
AdminFileInput.defaultProps = adminFileInputDefaultProps;

export { AdminFileInput };
