import api from "~/shared/services/api";

export const getDownloadableMaterial = ({ type, folderId = null }) =>
  Promise.resolve(
    api.get("/admin/downloadable_material", {
      params: { type, folder_id: folderId },
    })
  );

export const showDownloadableMaterial = materialId =>
  Promise.resolve(api.get(`/admin/downloadable_material/${materialId}`));

export const deleteDownloadableMaterial = materialId =>
  Promise.resolve(api.delete(`/admin/downloadable_material/${materialId}`));

export const createDownloadableMaterial = data =>
  Promise.resolve(api.post("/admin/downloadable_material", data));

export const updateDownloadableMaterial = (data, materialId) =>
  Promise.resolve(api.put(`/admin/downloadable_material/${materialId}`, data));

export const getMaterialTotalCount = ({
  startDate,
  endDate,
  materialId,
  granularity,
}) =>
  Promise.resolve(
    api.get("/admin/downloadable_material_log", {
      params: {
        start_date: startDate,
        end_date: endDate,
        downloadable_material_id: materialId,
        granularity,
      },
    })
  );

export const getMaterialUsersCountAccess = ({
  startDate,
  endDate,
  page,
  search,
  materialId,
}) =>
  Promise.resolve(
    api.get("/admin/downloadable_material_log/users/", {
      params: {
        start_date: startDate,
        page,
        search,
        end_date: endDate,
        downloadable_material_id: materialId,
      },
    })
  );
