import api from "~/shared/services/api";

export const getCertificatePreview = ({
  bodyMessage,
  background,
  footer,
  showSchedule,
  broadcastRoomIds,
}) =>
  Promise.resolve(
    api.get("/admin/preview/certificate", {
      params: {
        body_message: bodyMessage,
        background,
        footer,
        show_schedule: showSchedule,
        broadcast_room_ids: broadcastRoomIds,
      },
    })
  );
