import types from "./types";

// LIST
export function listCertificates() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listCertificatesSuccessful({ certificates }) {
  return {
    type: types.LIST.SUCCESS,
    payload: { certificates },
  };
}
export function listCertificatesFailed() {
  return {
    type: types.LIST.FAILED,
  };
}

// SHOW
export function showCertificate({ certificateId }) {
  return {
    type: types.SHOW.REQUEST,
    payload: { certificateId },
  };
}
export function showCertificateSuccessful({ certificate }) {
  return {
    type: types.SHOW.SUCCESS,
    payload: { certificate },
  };
}
export function showCertificateFailed() {
  return {
    type: types.SHOW.FAILED,
  };
}
export function clearCertificate() {
  return {
    type: types.SHOW.CLEAR,
  };
}

// CREATE
export function createCertificate({ certificateData }) {
  return {
    type: types.CREATE.REQUEST,
    payload: { certificateData },
  };
}
export function createCertificateSuccessful({ certificate }) {
  return {
    type: types.CREATE.SUCCESS,
    payload: { certificate },
  };
}
export function createCertificateFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateCertificate({ certificateId, certificateData }) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { certificateId, certificateData },
  };
}
export function updateCertificateSuccessful({ certificate }) {
  return {
    type: types.UPDATE.SUCCESS,
    payload: { certificate },
  };
}
export function updateCertificateFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteCertificate({ certificateId }) {
  return {
    type: types.DELETE.REQUEST,
    payload: { certificateId },
  };
}
export function deleteCertificateSuccessful({ certificateId }) {
  return {
    type: types.DELETE.SUCCESS,
    payload: { certificateId },
  };
}
export function deleteCertificateFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// EVENTS
export function clearEvents() {
  return {
    type: types.EVENTS.CLEAR,
  };
}
