import React from "react";
import { MdDesktopMac } from "react-icons/md";
import PropTypes from "prop-types";

//* APIs
import {
  showBroadcastRoomScheduleRate,
  createBroadcastRoomScheduleRate,
} from "~/modules/signed/modules/client/apis/broadcastRooms/schedule/rate";

//* CUSTOM IMPORTS
import { openBroadcastRoom } from "~/shared/routes/navigation";
import { Button, Typography, Trans } from "~/shared/components/atoms";
import { VideoCard } from "~/shared/components/organisms/VideoCard";
import { Container, Stars, Description } from "./styles";
import SpeakerList from "~/shared/components/organisms/lists/SpeakerViewList";
import { getFullDate, getFullHours } from "~/shared/utils/dates";
import { isListValid } from "~/shared/utils/validations";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";

function ScheduleDetails({ className, schedule, ...rest }) {
  const {
    title,
    speakers,
    start_time: startTime,
    end_time: endTime,
    description,
    id,
    broadcastRoom,
  } = schedule;

  //* STATES
  const [rate, setRate] = React.useState(0);
  const [isRateLoaded, setIsRateLoaded] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => {
    const show = async () => {
      await showBroadcastRoomScheduleRate({
        scheduleId: id,
      }).then(response => {
        setRate(response.data.rate);
        setIsRateLoaded(true);
      });
    };

    show();

    return () => {
      setIsRateLoaded(false);
      setRate(0);
    };
  }, [id]);

  const onStarClick = async rateValue => {
    await createBroadcastRoomScheduleRate({ rate: rateValue, scheduleId: id });
  };

  const getTimeRange = () =>
    `${getFullHours(startTime)} - ${getFullHours(endTime)}`;

  const renderVideoCardSubTitle = () => {
    if (!isListValid(speakers)) return null;

    let subTitle = `Com ${speakers[0].name}`;

    if (speakers.length > 1) subTitle += ` e mais ${speakers.length - 1}`;

    return subTitle;
  };

  return (
    <Container className={className}>
      <Typography className="bold subTitle" maxLines={5}>
        {title}
      </Typography>
      <Typography className="regular opaque">{`
      ${getFullDate(startTime, true)} |  ${getTimeRange()}`}</Typography>

      <Stars onClick={onStarClick} isLoaded={isRateLoaded} initialRate={rate} />

      <Description defaultValue={description} value={description} />

      <SpeakerList
        speakers={speakers}
        renderCustomButton={s => (
          <BookmarkButton
            apiId={s.id}
            apiType="speakers"
            isSelected={s.bookmark}
          />
        )}
      />

      <Button
        size="small"
        icon={<MdDesktopMac />}
        style={{ marginTop: 20 }}
        onClick={() =>
          openBroadcastRoom({
            broadcastRoomId: broadcastRoom.id,
            title: broadcastRoom.room.title,
          })
        }
      >
        <Trans translationKey="entities.broadcastRooms.watch">
          Assistir transmissão
        </Trans>
      </Button>

      <VideoCard
        withFilm
        style={{ marginTop: 20 }}
        title={broadcastRoom.room.title}
        subTitle={renderVideoCardSubTitle()}
        time={getTimeRange()}
        onClick={() =>
          openBroadcastRoom({
            broadcastRoomId: broadcastRoom.id,
            title: broadcastRoom.room.title,
          })
        }
        {...rest}
      />
    </Container>
  );
}

ScheduleDetails.propTypes = {
  className: PropTypes.string,
  schedule: PropTypes.object.isRequired,
};

ScheduleDetails.defaultProps = {
  className: null,
};

export default ScheduleDetails;
