import React from "react";

import {
  openAdminDownloadMaterials,
  openCreateDownloadMaterials,
} from "~/shared/routes/navigation";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { Spinner } from "~/shared/components/atoms";

import { useTranslation } from "~/shared/hooks/translation";

import { Container, DownloadMaterialForm } from "./styles";

function CreateDownloadMaterial() {
  const [isLoading, setIsLoading] = React.useState(true);

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  const { translate } = useTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.downloadMaterials.downloadableMaterials"),
        function: openAdminDownloadMaterials,
      },
      {
        id: 2,
        name: translate("entities.downloadMaterials.addMaterial"),
        function: openCreateDownloadMaterials,
      },
    ]);
    setIsLoading(false);
    setEditMode(true);

    return () => {
      setEditMode(false);
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return isLoading ? (
    <Spinner centered />
  ) : (
    <Container>
      <DownloadMaterialForm />
    </Container>
  );
}

export default CreateDownloadMaterial;
