import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { ROOM_VIEW_TYPE } from "~/shared/utils/types";
import {
  Container,
  Form,
  Fieldset,
  FieldsetTitle,
  Dropdown,
  Groups,
} from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function SegmentationTab({ viewType, setViewType, ...rest }) {
  const { translate } = useTranslation();

  function convertViewType() {
    switch (viewType) {
      case ROOM_VIEW_TYPE.PUBLIC:
        return translate("common.public");

      case ROOM_VIEW_TYPE.PRIVATE:
        return translate("common.private");

      case ROOM_VIEW_TYPE.HIDE:
        return translate("common.hide");

      default:
        return null;
    }
  }

  function handleOnChange(value) {
    setViewType(value);
  }

  const viewTypes = [
    { id: 1, name: translate("common.public"), value: ROOM_VIEW_TYPE.PUBLIC },
    { id: 2, name: translate("common.private"), value: ROOM_VIEW_TYPE.PRIVATE },
    { id: 3, name: translate("common.hide"), value: ROOM_VIEW_TYPE.HIDE },
  ];

  return (
    <Container>
      <Form>
        <Fieldset>
          <FieldsetTitle>
            {translate(
              "entities.downloadMaterials.segmentationTab.fieldsetTitle"
            )}
          </FieldsetTitle>
          <Dropdown
            name="view_type"
            options={viewTypes}
            noRegistration
            initialValue={convertViewType()}
            onChange={handleOnChange}
          />
        </Fieldset>
      </Form>
      <Groups {...rest} />
    </Container>
  );
}

SegmentationTab.propTypes = {
  viewType: PropTypes.string.isRequired,
  setViewType: PropTypes.func.isRequired,
};

export default SegmentationTab;
