import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  HoverWrapper,
  Avatar,
  Typography,
  Icon,
  Box,
  avatarPropTypes,
  avatarDefaultProps,
} from "../../../atoms";
import { SimpleMenu, simpleCellPropTypes } from "../../../molecules";
import { Container, Badge, Information } from "./styles";

const chatCellPropTypes = {
  variant: PropTypes.oneOf(["default", "accent"]),

  userName: PropTypes.string.isRequired,

  /** Render an <b>Icon</b> at right of username based on this props.
   * see all icon names into <b>shared/utils/IconUtils</b>
   */
  iconName: PropTypes.string,

  lastMessage: PropTypes.string,

  /** The URL of an avatar image for the <b>Avatar</b> */
  avatar: PropTypes.string,

  /** If this props has some number, add an <b>Badge</b> to display
   * the conter of the messages
   */
  messageCounter: PropTypes.number,

  lastMessageTime: PropTypes.string,

  /** Control all <b>SimpleMenu</b> props.
   * See all props into <b>Molecules/SimpleMenu</b>
   */
  simpleMenu: PropTypes.shape(simpleCellPropTypes),

  /** Callback that is triggered when user clicks into the hole Cell. */
  onClick: PropTypes.func,

  avatarBadgeColor: avatarPropTypes.badgeColor,
};

const chatCellDefaultProps = {
  variant: "default",
  iconName: null,
  lastMessage: null,
  avatar: null,
  messageCounter: 0,
  lastMessageTime: null,
  simpleMenu: null,
  onClick: () => {},
  avatarBadgeColor: avatarDefaultProps.badgeColor,
};

/** An cell to display an chat with some user. */
const ChatCell = ({
  variant,
  userName,
  iconName,
  lastMessage,
  avatar,
  messageCounter,
  lastMessageTime,
  simpleMenu,
  avatarBadgeColor,
  onClick,
  ...rest
}) => {
  const renderVariant = () => {
    switch (variant) {
      case "accent":
        return "accentBackground";

      default:
        return "background";
    }
  };

  return (
    <HoverWrapper
      hovered={variant === "accent"}
      variant={renderVariant()}
      borderRadius={8}
      onClick={onClick}
    >
      <Container {...rest}>
        <Avatar
          src={avatar}
          // variant="badged"
          // badgeColor={avatarBadgeColor}
          size={56}
        />

        <Information className="chat-cell-infos">
          <Box display="flex" alignItems="center">
            <Typography className="subTitle bold" maxLines={1}>
              {userName}
            </Typography>

            {iconName && (
              <Icon iconName={iconName} size={16} style={{ marginLeft: 4 }} />
            )}
          </Box>

          <Box display="flex" alignItems="center">
            {lastMessage && (
              <Typography className="opaque" maxLines={1}>
                {lastMessage}
              </Typography>
            )}

            {lastMessageTime && (
              <Typography
                className={`opaque small bold ${variant === "accent" &&
                  "accent"}`}
                style={{ width: 100, marginLeft: 4 }}
              >
                {`- ${lastMessageTime}`}
              </Typography>
            )}
          </Box>
        </Information>

        {messageCounter > 0 && (
          <Badge
            borderRadius={4}
            disableOutline
            variant="accent"
            size={16}
            counter={messageCounter}
          />
        )}

        {simpleMenu && (
          <SimpleMenu
            {...simpleMenu}
            className="chat-cell-menu"
            buttonIcon={<Icon iconName="menu" />}
            style={{ position: "absolute", bottom: 0, right: 12 }}
          />
        )}
      </Container>
    </HoverWrapper>
  );
};

ChatCell.propTypes = chatCellPropTypes;

ChatCell.defaultProps = chatCellDefaultProps;

export { ChatCell, chatCellPropTypes, chatCellDefaultProps };
