import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import MaterialModal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

//* CUSTOM IMPORTS
import { useTranslation } from "../../../hooks/translation";
import { Icon, IconButton } from "../../atoms";
import { Card, Container, Scroll, Title } from "./styles";

const useStyles = makeStyles(() => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
}));

const modalPropTypes = {
  /** Set Modal visibility */
  show: PropTypes.any,
  /** Close modal function */
  onClose: PropTypes.func,
  /** Callback when user clicks to Return */
  onReturn: PropTypes.func,
  /** Render a title */
  title: PropTypes.string,
  /** String color for the title */
  titleColor: PropTypes.string,
  /** Name of the modal class (string) */
  className: PropTypes.string,
  /** Render a description */
  children: PropTypes.node.isRequired,
  /** Set modal as scrollable */
  withScroll: PropTypes.bool,
  /** Object containing the style */
  style: PropTypes.object,
  /** Remove the close Button */
  disableClose: PropTypes.bool,
  /** Any element to be rendered at footer of this component */
  footerComponent: PropTypes.element,
};

const modalDefaultProps = {
  titleColor: null,
  show: false,
  onClose: () => {},
  onReturn: null,
  title: null,
  className: null,
  withScroll: false,
  style: null,
  disableClose: false,
  footerComponent: null,
};

/** Modal is commonly used for feedbacks. */
function Modal({
  show,
  onClose,
  title,
  titleColor,
  className,
  children,
  withScroll,
  style,
  disableClose,
  onReturn,
  footerComponent,
  ...rest
}) {
  const classes = useStyles();
  const { translate } = useTranslation();

  const renderChildren = () =>
    withScroll ? (
      <Scroll className="modal-scroll">{children}</Scroll>
    ) : (
      <Container className="modal-container">{children}</Container>
    );

  return (
    <MaterialModal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={classes.modal}
      open={show}
      onClose={onClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
      {...rest}
    >
      <Fade in={show}>
        <Card className={className} style={style}>
          <header>
            {onReturn && (
              <IconButton
                className="modal-return-button"
                title={translate("common.return")}
                onClick={onReturn}
              >
                <Icon iconName="arrowBack" />
              </IconButton>
            )}

            <Title
              className="accent bold tinyTitle centered modal-title"
              style={{ color: titleColor }}
            >
              {title}
            </Title>

            {!disableClose && (
              <IconButton
                className="modal-delete-button"
                title={translate("common.close")}
                onClick={onClose}
                style={{ position: "relative", zIndex: 2 }}
              >
                <Icon iconName="close" />
              </IconButton>
            )}
          </header>

          {renderChildren()}

          {footerComponent}

          {/* {hasFooter && (
            <FooterButtons style={{ margin: "16px" }} {...footerButtons} />
          )} */}
        </Card>
      </Fade>
    </MaterialModal>
  );
}

Modal.propTypes = modalPropTypes;

Modal.defaultProps = modalDefaultProps;

export { Modal, modalPropTypes, modalDefaultProps };
