import React from "react";
import { useParams } from "react-router-dom";

//* APIs
import { showMarketRoom } from "~/modules/signed/modules/client/apis/marketRooms";
import {
  getPartnerAvailableDates,
  showPartnerMeeting,
} from "~/modules/signed/modules/client/apis/meetings/partner";

//* CUSTOM IMPORTS
import { MeetingModal } from "./components/MeetingModal";
import { useUser } from "~/modules/signed/modules/client/hooks";
import MarketRoomHeader from "./components/MarketRoomHeader";
import MarketRoomDetails from "./components/MarketRoomDetails";
import { Spinner } from "~/shared/components/atoms";
import { Container } from "./styles";
import { openMySchedule } from "~/shared/routes/navigation";
import { useNotification } from "~/modules/signed/hooks/notifications";
import { useTranslation } from "~/shared/hooks/translation";

function MarketRoom() {
  const { translate } = useTranslation();
  const { partnerId } = useParams();
  const { setHeaderTitle } = useUser();
  const { showNotification } = useNotification();

  //* STATES
  const [partner, setPartner] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);
  /** This state store all partner available meeting dataes */
  const [availableDates, setAvailableDates] = React.useState(null);
  const [showMeetingModal, setShowMeetingModal] = React.useState(false);
  /** This state indicates if user has an scheduled meeting with partner */
  const [scheduledMeeting, setScheduledMeeting] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      const marketRoomResponse = await showMarketRoom({
        marketRoomId: partnerId,
      });
      const availableDatesResponse = await getPartnerAvailableDates({
        partnerId,
      });
      const { data: partnerMeeting } = await showPartnerMeeting({ partnerId });

      setScheduledMeeting(partnerMeeting);
      setAvailableDates(availableDatesResponse.data);
      setPartner(marketRoomResponse.data);
      setHeaderTitle(marketRoomResponse.data.title);
      setIsLoaded(true);
    };

    fetch();

    return () => {
      setHeaderTitle(null);
    };
  }, [partnerId, setHeaderTitle]);

  const handleOnMeetingCreate = newScheduledMeeting => {
    setShowMeetingModal(false);
    setScheduledMeeting(newScheduledMeeting);

    /** Display an notification that the meeting was creasted. */
    showNotification({
      icon: "schedule",
      title: translate("entities.meetings.scheduling"),
      description: `
      ${translate("entities.meetings.notification.part1")}
      ${partner.title}
      ${translate("entities.meetings.notification.part2")}`,
      onClick: () => openMySchedule(newScheduledMeeting.id),
      onLinkClick: () => openMySchedule(newScheduledMeeting.id),
      linkButton: translate("entities.meetings.visualize"),
    });
  };

  const handleOnScheduleButtonClick = () => {
    if (scheduledMeeting) openMySchedule(scheduledMeeting.id);
    else setShowMeetingModal(true);
  };

  return isLoaded ? (
    <Container>
      <MarketRoomHeader
        partner={partner}
        scheduledMeeting={scheduledMeeting}
        disableScheduleButton={!availableDates}
        onSchedule={handleOnScheduleButtonClick}
      />

      <MarketRoomDetails partner={partner} />

      {availableDates && partner && (
        <MeetingModal
          isLoaded={isLoaded}
          show={showMeetingModal}
          availableDates={availableDates}
          partner={partner}
          onCancel={() => setShowMeetingModal(false)}
          onClose={() => setShowMeetingModal(false)}
          onCreate={handleOnMeetingCreate}
        />
      )}
    </Container>
  ) : (
    <Spinner centered />
  );
}

export default MarketRoom;
