import types from "./types";

// LIST
export function listNotifications({ page }) {
  return {
    type: types.listNotifications.REQUEST,
    payload: { page },
  };
}
export function listNotificationsSuccessful({ notifications, page, lastPage }) {
  return {
    type: types.listNotifications.SUCCESSFUL,
    payload: { notifications, page, lastPage },
  };
}

// PAGIANTE
export function paginateNotifications() {
  return {
    type: types.paginateNotifications.REQUEST,
  };
}

// CREATE
export function createNotification(notification) {
  return {
    type: types.createNotification.REQUEST,
    payload: { notification },
  };
}
export function createNotificationSuccessful(notification) {
  return {
    type: types.createNotification.SUCCESSFUL,
    payload: { notification },
  };
}
export function createNotificationFailed() {
  return {
    type: types.createNotification.FAILED,
  };
}

// UPDATE
export function updateNotification(notification, notificationId) {
  return {
    type: types.updateNotification.REQUEST,
    payload: { notification, notificationId },
  };
}
export function updateNotificationSuccessful(notification) {
  return {
    type: types.updateNotification.SUCCESSFUL,
    payload: { notification },
  };
}

export function updateNotificationFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteNotification(notificationId) {
  return {
    type: types.deleteNotification.REQUEST,
    payload: { notificationId },
  };
}
export function deleteNotificationSuccessful(notificationId) {
  return {
    type: types.deleteNotification.SUCCESSFUL,
    payload: { notificationId },
  };
}
export function deleteNotificationFailed() {
  return {
    type: types.deleteNotification.FAILED,
  };
}

// FORM
export function clearNotifications() {
  return {
    type: types.clear.REQUEST,
  };
}
