import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, LoaderList, LoaderCell, Title } from "./styles";
import NetworkRoomLoader from "../cellLoaders/NetworkCellLoader";
import StartFragmentLoader from "../cellLoaders/StartFragmentLoader";
import BroadcastRoomLoader from "../cellLoaders/BroadcastCellLoader";

const loaderContainerPropTypes = {
  /** Title */
  title: PropTypes.any,

  /** Type of loader, accept one of the 5 values:
   *
   * liveRoom,
   * transmissionRoom,
   * networkRoom
   * diamondPartner amd
   * stars */
  type: PropTypes.any.isRequired,

  /** Number size of content */
  size: PropTypes.number,
  className: PropTypes.string,
  cellLoader: PropTypes.any,

  /** Orientation (row or column) */
  orientation: PropTypes.oneOf(["row", "column"]),

  /** gap size between components */
  gap: PropTypes.number,
};

const laoderContainerDefaultProps = {
  title: null,
  size: 3,
  className: null,
  cellLoader: null,
  orientation: "row",
  gap: 0,
};

/** LoaderContainer is commonly used if you need a list of <b>cellLoaders</b> */
function LoaderContainer({
  className,
  title,
  type,
  size,
  cellLoader,
  orientation,
  gap,
  ...rest
}) {
  // FUNCTIONS
  const setLoader = () => {
    switch (type) {
      case "liveRoom":
        return <BroadcastRoomLoader />;

      case "transmissionRoom":
        return <BroadcastRoomLoader />;

      case "networkRoom":
        return <NetworkRoomLoader />;

      case "diamondPartner":
        return <BroadcastRoomLoader />;

      case "stars":
        return <StartFragmentLoader />;

      default:
    }

    return null;
  };

  function setLoaders() {
    const loaderArray = [];

    for (let i = 0; i < size; i += 1) {
      loaderArray.push(i);
    }

    return loaderArray;
  }

  const loaders = setLoaders();

  return (
    <Container className={className}>
      {title && <Title>{title}</Title>}

      <LoaderList
        id="loader-list"
        orientation={orientation}
        gap={gap}
        {...rest}
      >
        {loaders.map(loader => (
          <LoaderCell className="loader-cell" key={loader}>
            {cellLoader || setLoader(loader)}
          </LoaderCell>
        ))}
      </LoaderList>
    </Container>
  );
}

LoaderContainer.propTypes = loaderContainerPropTypes;

LoaderContainer.defaultProps = laoderContainerDefaultProps;

export {
  LoaderContainer,
  loaderContainerPropTypes,
  laoderContainerDefaultProps,
};
