import React from "react";
import PropTypes from "prop-types";

//* APIs
import { uploadFile } from "../../apis/files";

//* CUSTOM IMPORTS
import { LabeledAvatarInput } from "~/shared/components/molecules";

const AdminLabeledAvatarInputPropTypes = {
  initialValue: PropTypes.string,
};

const AdminLabeledAvatarInputDefaultProps = {
  initialValue: null,
};

const AdminLabeledAvatarInput = ({ initialValue, ...rest }) => {
  //* STATES
  const [src, setSrc] = React.useState(initialValue || null);
  const [isUploading, setIsUploading] = React.useState(false);

  //* FUNCTIONS
  async function handleOnUpload(file) {
    setIsUploading(true);

    await uploadFile({ file }).then(response => {
      setSrc(response.data.url);
    });

    setIsUploading(false);
  }

  return (
    <LabeledAvatarInput
      {...rest}
      onChange={handleOnUpload}
      src={src}
      isLoading={isUploading}
    />
  );
};

AdminLabeledAvatarInput.propTypes = AdminLabeledAvatarInputPropTypes;

AdminLabeledAvatarInput.defaultProps = AdminLabeledAvatarInputDefaultProps;

export { AdminLabeledAvatarInput };
