import React from "react";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import {
  NotificationCardPropTypes,
  NotificationCardDefaultProps,
} from "../propTypes";
import { Icon } from "../../../atoms";
import NotificationTextLoader from "../../cellLoaders/FieldsetLoader";
import { useTranslation } from "../../../../hooks/translation";
import { DialogModal as DeleteModal } from "../..";
import {
  Card,
  Title,
  Description,
  NotificationIcon,
  Time,
  OptionsMenu,
  TouchableWrapper,
  UnreadLink,
} from "./styles";

const DefaultNotificationCard = ({
  title,
  description,
  icon,
  isReaded,
  time,
  onLinkClick,
  onClick,
  onDelete,
  isLoaded,
  hasLink,
  unreadLink,
  footerButtons,
  ...rest
}) => {
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();

  const fillItems = () => {
    const items = [
      {
        id: 0,
        name: translate("common.delete"),
        function: () => setShowDeleteModal(true),
        color: colors.redWrong,
      },
    ];
    return items;
  };

  return isLoaded ? (
    <>
      <TouchableWrapper
        hovered={!isReaded}
        variant={isReaded ? "background" : "accentBackground"}
        disableHoverEffect={!hasLink}
        onClick={hasLink ? onLinkClick : () => {}}
      >
        <Card onClick={onClick} isReaded={isReaded} {...rest}>
          <NotificationIcon iconName={icon} />
          <Title maxLines={2}>{title}</Title>

          <OptionsMenu
            buttonIcon={<Icon iconName="menu" />}
            fillItems={() => fillItems}
          />

          <Description maxLines={4}>{description}</Description>
          {hasLink && <UnreadLink>{unreadLink}</UnreadLink>}

          <Time>{time}</Time>
          {footerButtons && { footerButtons }}
        </Card>
      </TouchableWrapper>

      <DeleteModal
        show={showDeleteModal}
        title={`${translate("common.delete")}`}
        description={`${translate(
          "common.AreYouSureYouWantToDeleteNotification"
        )}?`}
        onCancel={() => setShowDeleteModal(false)}
        onClose={() => setShowDeleteModal(false)}
        onConfirm={() => {
          setShowDeleteModal(false);
          onDelete();
        }}
      />
    </>
  ) : (
    <Card isReaded={isReaded} {...rest}>
      <NotificationTextLoader
        style={{ height: 40, width: 200, marginTop: 16 }}
      />
      <NotificationTextLoader style={{ height: 40, width: 200 }} />
    </Card>
  );
};

DefaultNotificationCard.propTypes = NotificationCardPropTypes;

DefaultNotificationCard.defaultProps = NotificationCardDefaultProps;

export { DefaultNotificationCard, NotificationCardDefaultProps };
