import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { Icon } from "~/shared/components/atoms";
import { DynamicField } from "~/modules/signed/modules/client/components/DynamicField";
import { Container, Fieldset, IconButton } from "./styles";

function DropdownAttributeCell({ attribute, onDelete, onChange }) {
  const { title } = attribute;

  const { translate } = useTranslation();

  return (
    <Container>
      <Fieldset>
        <Icon iconName="drag" />

        {title && (
          <DynamicField
            inputType="text"
            name={String(title)
              .toLocaleLowerCase()
              .trim()}
            placeholder={translate("components.input.option.placeholder")}
            defaultValue={title}
            onChange={e => onChange({ ...attribute, title: e.target.value })}
          />
        )}
        <IconButton
          title={translate("entities.registrationFields.delete")}
          type="button"
          onClick={() => onDelete(attribute.id)}
        >
          <Icon iconName="close" id="close-button" />
        </IconButton>
      </Fieldset>
    </Container>
  );
}

DropdownAttributeCell.propTypes = {
  attribute: PropTypes.object.isRequired,
  onDelete: PropTypes.func,
};

DropdownAttributeCell.defaultProps = {
  onDelete: () => {},
};

export default DropdownAttributeCell;
