import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { INPUT_TYPES } from "~/shared/utils/inputs";
import { DynamicInput } from "./components/DynamicInput";
import { PhoneInput } from "~/shared/components/atoms";
import { DynamicDropdown } from "./components/DynamicDropdown";
import { DynamicTags } from "./components/DynamicTags";
import { Container } from "./styles";

const dynamicFieldPropTypes = {
  /** This props control, if the <b>DynamicField</b> will render an normal <b>Input</b>,
   * or an <b>DynamicDropdown</b>, or even an <b>TextArea</b>, all inputTypes can be found
   * in <b>~/shared/utils/InputUtils</b>. Default value is 'text' */
  inputType: PropTypes.oneOf(
    Object.keys(INPUT_TYPES).map(key => INPUT_TYPES[key].value)
  ),

  /** This props is required if you render an <b>DynamicDropdown</b>, the <b>refCode</b>
   * is needed to call the API that will list all the options for that refCode <b>DynamicDropdown</b>.
   */
  refCode: PropTypes.string,

  /** If you want to populate the <b>DynamicDropdown</b> by yourself.  */
  dropdownOptions: PropTypes.arrayOf(PropTypes.object),

  /** Name is required to reference the input inside the form  */
  name: PropTypes.string.isRequired,

  /** Add an <b>*</b> in the input title */
  isRequired: PropTypes.bool,

  /** If TRUE change the input for an textArea */
  isTextArea: PropTypes.bool,

  /** Add an title above input */
  title: PropTypes.string,

  /** Add an icon in the left */
  icon: PropTypes.string,

  /** If index exists, will set an zIndex to control overflow layer, when
   * form has many input one above another
   */
  index: PropTypes.number,

  /** Force an type for the input, accept all the default type of an <b>HTMLInput</b> */
  type: PropTypes.string,

  className: PropTypes.string,

  style: PropTypes.object,

  /** Define the max value for an input */
  max: PropTypes.number,
};

const dynamicFieldDefaultProps = {
  isTextArea: false,
  title: "",
  icon: null,
  dropdownOptions: null,
  index: null,
  type: null,
  isRequired: false,
  inputType: INPUT_TYPES.TEXT.value,
  refCode: null,
  className: null,
  style: null,
  max: null,
};

/** DynamicField can be an <b>DynamicDropdown</b>, <b>TextArea</b> or an default <b>Input</b> component,
 * base on <b>inputType</b> props, he made all the validations to render the correct input.
 */
function DynamicField({
  name,
  index,
  title,
  className,
  icon,
  type,
  inputType,
  refCode,
  isRequired,
  dropdownOptions,
  style,
  max,
  ...rest
}) {
  const isDropdown =
    inputType === INPUT_TYPES.SELECT.value ||
    inputType === INPUT_TYPES.COUNTRY.value ||
    inputType === INPUT_TYPES.STATE.value ||
    inputType === INPUT_TYPES.CITY.value;

  const isPhone = inputType === INPUT_TYPES.PHONE.value;

  const isTags = inputType === INPUT_TYPES.TAGS.value;

  //* FUNCTIONS
  const renderField = () => {
    if (isDropdown)
      return (
        <DynamicDropdown
          title={title}
          isRequired={isRequired}
          name={name}
          inputType={inputType}
          icon={icon}
          refCode={refCode}
          dropdownOptions={dropdownOptions}
          {...rest}
        />
      );

    if (isPhone)
      return (
        <PhoneInput
          title={title}
          name={name}
          icon={icon}
          isRequired={isRequired}
          index={index}
          {...rest}
        />
      );

    if (isTags)
      return (
        <DynamicTags
          title={title}
          name={name}
          refCode={refCode}
          initialData={dropdownOptions || []}
          max={max}
          {...rest}
        />
      );

    return (
      <DynamicInput
        title={title}
        name={name}
        icon={icon}
        type={type}
        inputType={inputType}
        isRequired={isRequired}
        {...rest}
      />
    );
  };

  return (
    <Container
      className={className}
      index={inputType === INPUT_TYPES.TAGS.value ? 100 + index : 100 - index}
      style={style}
    >
      {renderField()}
    </Container>
  );
}

DynamicField.propTypes = dynamicFieldPropTypes;

DynamicField.defaultProps = dynamicFieldDefaultProps;

export { DynamicField, dynamicFieldDefaultProps, dynamicFieldPropTypes };
