import React from "react";
import { FaUsers } from "react-icons/fa";
import { MdFileUpload } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import { listUsers } from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

//* CUSTOM IMPORTS

import { useCMS } from "~/modules/signed/modules/cms/hooks";
import {
  openCreateUser,
  openAdminUserImport,
} from "~/shared/routes/navigation";
import Loader from "~/shared/components/molecules/cellLoaders/SingleBarLoader";
import UserCell from "~/modules/signed/modules/cms/components/cells/UserCell";
import { isListValid } from "~/shared/utils/validations";
import { Empty, SearchBar } from "~/shared/components/molecules";
import { IconedButton, Box } from "~/shared/components/atoms";
import {
  Container,
  Header,
  CardHeader,
  CardWrapper,
  Card,
  Users,
  Pagination,
  ContentLoader,
  CellLoader,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function UserList() {
  const dispatch = useDispatch();
  const { data, isLoaded } = useSelector(state => state.adminUsers.list);
  const { data: users, perPage, lastPage, page } = data;

  //* STATES
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS
  const { translate } = useTranslation();
  const { scrollToTop } = useCMS();

  React.useEffect(() => {
    dispatch(listUsers({ search }));
  }, [dispatch, search]);

  function handlePagination(p) {
    dispatch(listUsers({ page: p, search }));
  }

  return (
    <Container>
      <Header>
        <Box display="flex">
          <IconedButton
            placeholder={translate("entities.users.add")}
            onClick={openCreateUser}
          />

          <IconedButton
            placeholder={translate("entities.users.import")}
            onClick={openAdminUserImport}
            icon={<MdFileUpload />}
            style={{ marginLeft: 32 }}
          />
        </Box>

        <SearchBar
          isSearching={!isLoaded}
          onChange={value => setSearch(value)}
          placeholder={translate("entities.users.search")}
          style={{ maxWidth: 300 }}
        />
      </Header>

      {isLoaded && !isListValid(users) ? (
        <Empty
          icon={<FaUsers />}
          search={search}
          variant={search && "search"}
          message={translate("entities.users.empty.message")}
          description={translate("entities.users.empty.description")}
        />
      ) : (
        <CardWrapper>
          <CardHeader>
            <p>{translate("common.name")}</p>
            <p>{translate("common.email")}</p>
          </CardHeader>

          <Card isLoading={!isLoaded}>
            {isLoaded ? (
              <Users>
                {isListValid(users) &&
                  users.map(user => <UserCell key={user.id} user={user} />)}
              </Users>
            ) : (
              <ContentLoader
                type="adminUser"
                size={5}
                cellLoader={
                  <CellLoader>
                    <div className="user-loader">
                      <Loader />
                    </div>

                    <div className="user-loader">
                      <Loader />
                    </div>
                  </CellLoader>
                }
              />
            )}
          </Card>

          {isLoaded && (
            <Pagination
              perPage={perPage}
              lastPage={lastPage}
              currentPage={page}
              handlePagination={p => {
                scrollToTop();
                handlePagination(p);
              }}
            />
          )}
        </CardWrapper>
      )}
    </Container>
  );
}

export default UserList;
