import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { CellList, SimpleMenu, simpleMenuPropTypes } from "../../molecules";
import { Box, Typography, IconButton, Checkbox, Icon } from "../../atoms";
import { Container, Item, SimpleMenuChildren } from "./styles";

const matchInfoCardPropTypes = {
  /** Renders the title of the card */
  title: PropTypes.string.isRequired,

  variant: PropTypes.oneOf(["error", "checked", "warning"]),

  /** Callback that is triggered when user clicks into EditButton */
  onEdit: PropTypes.func,

  /** Control all <b>SimpleMenu</b> props, see more in Molecules/SimpleMenu,
   * this props is needed if the variant of the component is "error"
   */
  simpleMenu: PropTypes.shape(simpleMenuPropTypes),

  /** An array of string thats render an list inside the card  */
  items: PropTypes.arrayOf(PropTypes.string),

  /** When an item of the list is not valid render itemEmpty component
   * instead of the current item
   */
  itemEmpty: PropTypes.element,
};

const matchInfoCardDefaultProps = {
  simpleMenu: null,
  items: [],
  onEdit: () => {},
  itemEmpty: null,
  variant: "checked",
};

const MatchInfoCard = ({
  title,
  variant,
  items,
  itemEmpty,
  onEdit,
  simpleMenu,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);

  const isErrored = variant === "error";
  const isWarned = variant === "warning";

  return (
    <Container variant={variant} {...rest}>
      <Box
        display="flex"
        alignItems="center"
        padding="4px"
        justifyContent="space-between"
      >
        <Box display="flex" alignItems="center" maxWidth="calc(100% - 46px)">
          <IconButton className="match-info-card-edit" onClick={onEdit}>
            <Icon iconName="edit" />
          </IconButton>

          <Typography className="subTitle bold accent capitalize" maxLines={1}>
            {title}
          </Typography>
        </Box>

        {isErrored || isWarned ? (
          <SimpleMenu
            position="left"
            alignment="center"
            buttonIcon={<Icon iconName={isWarned ? "warning" : "error"} />}
            buttonIconColor={isWarned ? colors.yellowWarning : colors.redWrong}
            {...simpleMenu}
          >
            <SimpleMenuChildren>{simpleMenu.children}</SimpleMenuChildren>
          </SimpleMenu>
        ) : (
          <Checkbox checked={variant === "checked"} />
        )}
      </Box>

      <CellList
        isLoaded
        listStyle={{ padding: "0 16px" }}
        data={items}
        orientation="column"
        cellStyle={{ maxWidth: "100%" }}
        renderCell={item => (
          <Item maxLines={1} key={item}>
            {item || itemEmpty}
          </Item>
        )}
      />
    </Container>
  );
};

MatchInfoCard.propTypes = matchInfoCardPropTypes;
MatchInfoCard.defaultProps = matchInfoCardDefaultProps;

export { MatchInfoCard, matchInfoCardPropTypes, matchInfoCardDefaultProps };
