import types from "./types";

// LIST
export function listSpeakers() {
  return {
    type: types.listSpeakers.REQUEST,
  };
}
export function listSpeakersSuccessful(speakers) {
  return {
    type: types.listSpeakers.SUCCESSFUL,
    payload: { speakers },
  };
}

// CREATE
export function createSpeaker(speaker) {
  return {
    type: types.createSpeaker.REQUEST,
    payload: { speaker },
  };
}
export function createSpeakerSuccessful(speaker) {
  return {
    type: types.createSpeaker.SUCCESSFUL,
    payload: { speaker },
  };
}
export function createSpeakerFailed() {
  return {
    type: types.createSpeaker.FAILED,
  };
}

// EDIT
export function updateSpeaker(speakerId, speaker) {
  return {
    type: types.updateSpeaker.REQUEST,
    payload: { speakerId, speaker },
  };
}
export function updateSpeakerSuccessful(speaker) {
  return {
    type: types.updateSpeaker.SUCCESSFUL,
    payload: { speaker },
  };
}
export function updateSpeakerFailed() {
  return {
    type: types.updateSpeaker.FAILED,
  };
}

// DELETE
export function deleteSpeaker(speakerId) {
  return {
    type: types.deleteSpeaker.REQUEST,
    payload: { speakerId },
  };
}
export function deleteSpeakerSuccessful(speakerId) {
  return {
    type: types.deleteSpeaker.SUCCESSFUL,
    payload: { speakerId },
  };
}
export function deleteSpeakerFailed() {
  return {
    type: types.deleteSpeaker.FAILED,
  };
}

// CLEAR
export function clearSpeakerApiStatus() {
  return {
    type: types.clear.REQUEST,
  };
}
