import React from "react";
import PropTypes from "prop-types";
import PerfectScroll from "react-perfect-scrollbar";

const scrollPropTypes = {
  children: PropTypes.element.isRequired,
};

/** The default Scroll of application.
 *
 * The Scroll component use <b>react-perfect-scrollbar</b> lib under the hood
 *
 * see all props here: https://github.com/goldenyz/react-perfect-scrollbar
 */
const Scroll = ({ children, ...rest }) => (
  <PerfectScroll {...rest}>{children}</PerfectScroll>
);

Scroll.propTypes = scrollPropTypes;

export { Scroll, scrollPropTypes };
