import types from "./types";

// LIST
export function listUsers({ page = 1, search = null }) {
  return {
    type: types.LIST.REQUEST,
    payload: { page, search },
  };
}
export function listUsersSuccessful(users) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { users },
  };
}
export function clearUsers() {
  return {
    type: types.LIST.CLEAR,
  };
}

// SHOW
export function showUser(userId) {
  return {
    type: types.SHOW.REQUEST,
    payload: { userId },
  };
}
export function showUserSuccessful(user) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { user },
  };
}
export function clearUser() {
  return {
    type: types.SHOW.CLEAR,
  };
}

// CREATE
export function createUser(user) {
  return {
    type: types.CREATE.REQUEST,
    payload: { user },
  };
}
export function createUserSuccessful(user) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { user },
  };
}
export function createUserFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateUser(user, userId, disableMessage = false) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { user, userId, disableMessage },
  };
}
export function updateUserSuccessful(user) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { user },
  };
}
export function updateUserFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteUser(userId) {
  return {
    type: types.DELETE.REQUEST,
    payload: { userId },
  };
}
export function deleteUserSuccessful(userId) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { userId },
  };
}

export function deleteUserFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// FORM
export function clearEvents() {
  return {
    type: types.FORM.CLEAR,
  };
}

// ACTIVITIES
export function listUserActivities({ userId, page = 1 }) {
  return {
    type: types.ACTIVITIES.REQUEST,
    payload: { userId, page },
  };
}
export function listUserActivitiesSuccessful({ activities, page }) {
  return {
    type: types.ACTIVITIES.SUCCESSFUL,
    payload: { activities, page },
  };
}
export function clearUserActivities() {
  return {
    type: types.ACTIVITIES.CLEAR,
  };
}
