import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS
import { SearchBar } from "../../styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { PosterCell } from "~/shared/components/organisms/cells";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";
import { openPosterDetails } from "~/shared/routes/navigation";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";

function Posters({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkPosters,
    isSearching,
    postersCurrentPage,
    searchString,
    paginateBookmarks,
    listBookmarks,
    setBookmarkPosters,
    setSearchString,
  } = useBookmarkProfile();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (!bookmarkPosters.isLoaded && isFetcheable)
      listBookmarks(bookmarkPosters.page, "posters", setBookmarkPosters);
  }, [
    bookmarkPosters.isLoaded,
    bookmarkPosters.page,
    isFetcheable,
    listBookmarks,
    setBookmarkPosters,
  ]);

  // FUNCTIONS
  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="paragraph" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="paragraph" />}
      />
    );
  };

  const renderCell = poster => {
    const {
      title,
      description,
      author,
      co_author: coAuthors,
      id,
      bookmark,
    } = poster;

    const coAuthorsSize = !coAuthors ? [] : String(coAuthors).split(",");

    return (
      <PosterCell
        customButton={
          <BookmarkButton
            isSelected={bookmark}
            apiType="posters"
            apiId={id}
            variant="icon"
          />
        }
        entity={translate("entities.poster.singular")}
        entityName={title}
        title={title}
        footerDescription={`${translate("common.per")} ${author}  ${
          coAuthorsSize.length > 0
            ? `${translate("common.andMore")} ${coAuthorsSize.length}`
            : ""
        }`}
        description={description}
        onClick={() => openPosterDetails(poster.id)}
      />
    );
  };
  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);

          listBookmarks(
            bookmarkPosters.page,
            "posters",
            setBookmarkPosters,
            value
          );
        }}
      />

      <CellList
        isLoaded={isUserLoaded && !isSearching}
        isPaging={bookmarkPosters.isPaging}
        renderCellLoader={() => (
          <CellLoader width={298} height={160} style={{ width: 298 }} />
        )}
        loaderListSize={3}
        gap={20}
        data={bookmarkPosters.data}
        renderCell={room => renderCell(room)}
        emptyComponent={handleEmptyMessage()}
      />

      <Pagination
        style={{ marginTop: 16 }}
        currentPage={postersCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.ROOMS, page);
        }}
        lastPage={bookmarkPosters.lastPage}
      />
    </div>
  );
}

Posters.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { Posters };
