import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import EmailForm from "../../components/EmailForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminEmails } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminEmail } from "../../hooks";
import { Spinner } from "~/shared/components/atoms";

function EditEmail() {
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs } = useCMS();
  const {
    email,
    showAdminEmail,
    isEmailLoaded,
    clearAdminEmail,
  } = useAdminEmail();

  // PARAMS
  const { emailId } = useParams();

  // FUNCTIONS

  React.useEffect(() => {
    if (emailId) showAdminEmail(emailId);

    return () => {
      clearAdminEmail();
    };
  }, [clearAdminEmail, emailId, showAdminEmail]);

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.emails.plural"),
        function: openAdminEmails,
      },
      {
        id: 2,
        name: translate("entities.emails.new"),
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, translate]);

  return isEmailLoaded ? <EmailForm email={email} /> : <Spinner centered />;
}

export default EditEmail;
