import { takeLatest, all } from "redux-saga/effects";

//* CUSTOM IMPORTS
import { subscribe } from "~/shared/services/webSocket";
import types from "./types";

export function* sendBroadcastRoomReaction({ payload }) {
  try {
    const { broadcastRoomId, emojiType } = payload;

    const channel = yield subscribe(`reaction:${broadcastRoomId}`);

    channel.emit("sendReaction", {
      type: "broadcast_room",
      ref_id: broadcastRoomId,
      emoji_type: emojiType,
    });
  } catch (err) {
    console.log(err);
  }
}

export function* sendBroadcastRoomChatReaction({ payload }) {
  try {
    const { broadcastRoomId, emojiType, messageId } = payload;

    const channel = yield subscribe(`reaction:${broadcastRoomId}`);

    channel.emit("sendReaction", {
      type: "message",
      ref_id: messageId,
      emoji_type: emojiType,
    });
  } catch (err) {
    console.log(err);
  }
}

export function* removeBroadcastRoomChatReaction({ payload }) {
  try {
    const { broadcastRoomId, messageId } = payload;

    const channel = yield subscribe(`reaction:${broadcastRoomId}`);

    channel.emit("removeReaction", {
      type: "message",
      ref_id: messageId,
    });
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest(
    types.channel.broadcastRoom.SEND_REACTION,
    sendBroadcastRoomReaction
  ),
  takeLatest(
    types.channel.broadcastRoomChat.SEND_REACTION,
    sendBroadcastRoomChatReaction
  ),
  takeLatest(
    types.channel.broadcastRoomChat.REMOVE_REACTION,
    removeBroadcastRoomChatReaction
  ),
]);
