// UPDATE PROFILE
export function updateProfile({ userData, userId }) {
  return {
    type: "@user/UPDATE_PROFILE",
    payload: { userData, userId },
  };
}

export function updateProfileSuccessful(user) {
  return {
    type: "@user/UPDATE_PROFILE_SUCCESSFUL",
    payload: { user },
  };
}

export function clearUpdatingProfile() {
  return {
    type: "@user/CLEAR_UPDATE_PROFILE",
  };
}

// REFRESH PROFILE
export function refreshProfile(userId = null) {
  return {
    type: "@user/REFRESH_PROFILE",
    payload: { userId },
  };
}
export function refreshProfileSuccessful(user) {
  return {
    type: "@user/REFRESH_PROFILE_SUCCESSFUL",
    payload: { user },
  };
}

// COMPLETE PROFILE
export function completeSignUp({ userData, userId }) {
  return {
    type: "@user/COMPLETE_SIGNUP",
    payload: { userData, userId },
  };
}

export function completeSignUpSuccessful(user) {
  return {
    type: "@user/COMPLETE_SIGNUP_SUCCESSFUL",
    payload: { user },
  };
}

// UPDATE AVATAR
export function updateAvatar(file, userId) {
  return {
    type: "@user/UPDATE_AVATAR",
    payload: { file, userId },
  };
}

export function updateAvatarSuccessful(user) {
  return {
    type: "@user/UPDATE_AVATAR_SUCCESSFUL",
    payload: { user },
  };
}

export function userAPIError() {
  return {
    type: "@user/ERROR",
  };
}
