import chatTypes from "./types";

export function receiveMessages(messages) {
  return {
    type: chatTypes.channel.RECEIVE_MESSAGES,
    payload: { messages },
  };
}

export function receiveSingleMessage(message) {
  return {
    type: chatTypes.channel.RECEIVE_SINGLE_MESSAGE,
    payload: { message },
  };
}

export function receiveSingleMessageLikes({ message }) {
  return {
    type: chatTypes.channel.RECEIVE_SINGLE_MESSAGE_LIKES,
    payload: { message },
  };
}

export function sendMessage({
  message,
  user,
  broadcastRoomId,
  messageRepliedId,
}) {
  return {
    type: chatTypes.channel.SEND_MESSAGE,
    payload: { message, user, broadcastRoomId, messageRepliedId },
  };
}

export function sendPinnedMessage({ messageId, broadcastRoomId }) {
  return {
    type: chatTypes.channel.SEND_PINNED_MESSAGE,
    payload: { messageId, broadcastRoomId },
  };
}

export function receivePinnedMessage({ pinnedMessage }) {
  return {
    type: chatTypes.channel.RECEIVE_PINNED_MESSAGE,
    payload: { pinnedMessage },
  };
}

export function receiveBlockedUsers(blockedUsers) {
  return {
    type: chatTypes.channel.RECEIVE_BLOCKED_USERS,
    payload: { blockedUsers },
  };
}

export function openChatMessageOptions() {
  return {
    type: chatTypes.options.OPEN,
  };
}

export function closeChatMessageOptions() {
  return {
    type: chatTypes.options.CLOSE,
  };
}

export function blockUserChat({ userId, broadcastRoomId }) {
  return {
    type: chatTypes.channel.admin.BLOCK_USER,
    payload: { userId, broadcastRoomId },
  };
}

export function deleteMessage({ messageId }) {
  return {
    type: chatTypes.channel.admin.DELETE_MESSAGE,
    payload: { messageId },
  };
}

export function deleteMessageSucessful({ messageId }) {
  return {
    type: chatTypes.channel.admin.DELETE_MESSAGE_SUCCESSFUL,
    payload: { messageId },
  };
}

export function deleteMessageFailed() {
  return {
    type: chatTypes.channel.admin.DELETE_MESSAGE_FAILED,
  };
}

export function clearChat() {
  return {
    type: chatTypes.CLEAR,
  };
}
