import React from "react";

//* APIs
import { listCategories } from "~/modules/signed/modules/client/apis/categories";

//* CUSTOM IMPORTS
import { useWindow } from "~/shared/hooks/window";
import { useTranslation } from "~/shared/hooks/translation";
import { CATEGORY_TYPES } from "~/shared/utils/types";
import { Typography, Spinner, Trans } from "~/shared/components/atoms";
import { SearchBar, StaticTabs } from "~/shared/components/molecules";
import {
  Container,
  Content,
  ScheduleList,
  ScheduleDetails,
  ScheduleDetailsMobile,
  ScheduleDetailsScroll,
} from "./styles";

function Schedule() {
  const { windowWidth } = useWindow();
  const { translate } = useTranslation();

  //* STATES
  const [search, setSearch] = React.useState("");
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [categories, setCategories] = React.useState([]);
  const [showDetails, setShowDetails] = React.useState(false);
  const [selectedSchedule, setSelectedSchedule] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      await listCategories({ type: CATEGORY_TYPES.BROADCAST_ROOM }).then(
        response => {
          setCategories(response.data);
          setIsLoaded(true);
        }
      );
    };

    list();
  }, []);

  const changeDetails = schedule => {
    setSelectedSchedule(null);

    //* Use setTimeout to force PlyrPlayer re render to change the video src
    setTimeout(() => setSelectedSchedule(schedule), 1);
  };

  const openDetails = schedule => {
    setSelectedSchedule(schedule);
    setShowDetails(true);
  };

  const closeDetails = () => {
    setSelectedSchedule(null);
    setShowDetails(false);
  };

  if (!isLoaded) return <Spinner centered />;

  return (
    <Container>
      <Typography className="bigTitle bold" style={{ marginBottom: 16 }}>
        <Trans translationKey="pages.homeSchedule.title">Programação</Trans>
      </Typography>

      <SearchBar
        centerPlaceholder
        className="maxWidth"
        variant="contained"
        placeholder={translate("pages.homeSchedule.searchBar.placeholder")}
        style={{ marginBottom: 20 }}
        onChange={value => setSearch(value)}
      />

      <Content isScheduleSelected={showDetails}>
        <StaticTabs
          tabs={categories}
          className="static-tabs min-width"
          onChange={() => closeDetails()}
        >
          {categories.map(category => (
            <ScheduleList
              noScrollReposition
              withScroll={windowWidth >= 1024}
              scrollStyle={{
                padding: "0 16px",
                overflowX: "hidden",
              }}
              search={search}
              categoryId={category.id}
              onCellClick={schedule => {
                if (!selectedSchedule) {
                  openDetails(schedule);
                } else if (schedule.id === selectedSchedule.id) {
                  closeDetails(null);
                } else {
                  changeDetails(schedule);
                }
              }}
              setSelectedSchedule={setSelectedSchedule}
              selectedSchedule={selectedSchedule}
              cellFooterElement={
                selectedSchedule && (
                  <ScheduleDetailsMobile
                    schedule={selectedSchedule}
                    videoPlayer={{
                      muted: true,
                      autoPlay: true,
                      //* To handle multiple players in the same screen Plyr need an unique id
                      plyrId: `schedulePlayer${selectedSchedule &&
                        selectedSchedule.id}`,
                      src:
                        selectedSchedule &&
                        selectedSchedule.broadcastRoom.video_player,
                      type:
                        selectedSchedule && selectedSchedule.broadcastRoom.type,
                    }}
                  />
                )
              }
            />
          ))}
        </StaticTabs>

        {showDetails && selectedSchedule && (
          <ScheduleDetailsScroll className="min-width">
            <ScheduleDetails
              schedule={selectedSchedule}
              videoPlayer={{
                muted: true,
                autoPlay: true,
                plyrId: "schedulePlayer",
                src:
                  selectedSchedule &&
                  selectedSchedule.broadcastRoom.video_player,
                type: selectedSchedule && selectedSchedule.broadcastRoom.type,
                onLive: selectedSchedule.broadcastRoom.on_live,
              }}
            />
          </ScheduleDetailsScroll>
        )}
      </Content>
    </Container>
  );
}

export default Schedule;
