import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import noPicture from "../../../assets/static/noPicture.png";
import { badgePropTypes } from "../Badge";
import { Container, AvatarImage, Badge } from "./styles";

const avatarPropTypes = {
  className: PropTypes.string,
  /** Source of an image */
  src: PropTypes.string,

  /** The size in <b>px</b> for this component */
  size: PropTypes.number,

  variant: PropTypes.oneOf(["badged", "default"]),

  /** The  hexadecimal color for <b>Badge</b> component.
   *
   * <i>obs: This props only works in <b>badged</b> variant.</i>
   */
  badgeColor: badgePropTypes.color,

  /** The  hexadecimal color for <b>Badge</b> outline component.
   *
   * <i>obs: This props only works in <b>badged</b> variant.</i>
   */
  badgeOutlineColor: badgePropTypes.outlineColor,

  /** An source of an image to be the Frame of Avatar. */
  frame: PropTypes.string,
};

const avatarDefaultProps = {
  className: null,
  src: null,
  size: 40,
  variant: "default",
  badgeColor: null,
  badgeOutlineColor: null,
  frame: null,
};

/** Avatar commonly used to represent a user and can contain photos */
function Avatar({
  src,
  size,
  variant,
  badgeColor,
  badgeOutlineColor,
  frame,
  ...rest
}) {
  return (
    <Container {...rest} frame={frame} size={size}>
      <AvatarImage
        className="avatar-image"
        size={size}
        src={src || noPicture}
      />

      {variant === "badged" && (
        <Badge
          size={size * 0.255}
          color={badgeColor}
          outlineColor={badgeOutlineColor}
        />
      )}
    </Container>
  );
}

Avatar.propTypes = avatarPropTypes;

Avatar.defaultProps = avatarDefaultProps;

export { Avatar, avatarPropTypes, avatarDefaultProps };
