import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import types from "./types";
import adminRegistrationFieldsTypes from "~/modules/signed/modules/cms/modules/admin/store/registrationFields/types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
  },

  form: {
    stateId: null,
    city: {},
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { registrationFields } = action.payload;

        if (isDataValid(registrationFields)) {
          draft.list.data = registrationFields;
        }

        draft.list.isLoaded = true;

        break;
      }
      case types.LIST.CLEAR: {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }

      // FORM
      case types.FORM.SET_STATE: {
        const { stateId } = action.payload;

        draft.form.stateId = stateId;

        break;
      }

      case types.FORM.SET_CITY: {
        const { city } = action.payload;

        draft.form.city = city;

        break;
      }

      // ADMIN REGISTRATION FIELDS
      case adminRegistrationFieldsTypes.CREATE.SUCCESSFUL: {
        const { registrationField } = action.payload;

        if (isDataValid(registrationField))
          draft.list.data = [...draft.list.data, registrationField];

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isLoaded = false;

        draft.form.stateId = null;
        draft.form.city = {};

        break;
      }
      default:
    }
  });
}
