import React from "react";

//* CONTEXT AND PROVIDER
import {
  BroadcastRoomsProvider,
  useBroadcastRoomContext,
} from "./context/brodcastRoomContext";

//* CUSTOM IMPORTS
import { CATEGORY_TYPES } from "~/shared/utils/types";
import CategoryModal from "~/modules/signed/modules/cms/components/modals/CategoryModal";
import {
  Container,
  AddCategoryButton,
  Header,
  SearchBar,
  CategoryHeader,
  AddButton,
} from "./styles";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { Icon, IconButton, Typography } from "~/shared/components/atoms";
import { Empty, LoaderContainer } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "../../../../../../hooks/dynamicTranslation";
import { BroadcastRoomCell } from "~/shared/components/organisms/cells";
import { DraggableBoard } from "~/shared/components/organisms/DraggableBoard";

function Content() {
  //* STATES
  const [categoryData, setCategoryData] = React.useState(null);
  const [showModal, setShowModal] = React.useState(false);
  const {
    categoriesList,
    isLoaded,
    setSearch,
    isSearching,
    handleDeleteBroadcastRoom,
    handleEditBroadcastRoom,
    handleCreateBroadcastRoom,
    handleDragItemToAnotherColumn,
    handleMoveItemOfColumn,
    handleReorderCategories,
    handleDeleteCategory,
    handleCreateCategory,
    handleUpdateCategory,
  } = useBroadcastRoomContext();

  //* FUNCTION
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  const openModal = () => setShowModal(true);

  const closeModal = () => setShowModal(false);

  const onCellEditClick = category => {
    setCategoryData(category);
    openModal();
  };

  const handleCreateCategories = () => {
    openModal();
  };

  const { dTranslateTags, dTranslatePartners } = useDynamicTranslation();

  const renderCell = (broadcastRoom, category) => {
    const {
      id,
      room,
      __meta__,
      open_live: openLive,
      end_live: endLive,
      on_live: onLive,
    } = broadcastRoom;

    return (
      <BroadcastRoomCell
        style={{ margin: "0 8px" }}
        isAdmin
        id={room.id}
        image={room.logo}
        title={dTranslate(room.title)}
        tags={dTranslateTags(room.tags)}
        bookmark={room.bookmark}
        speakers={__meta__.speakers}
        startDate={openLive}
        endDate={endLive}
        onClick={() => handleEditBroadcastRoom(id)}
        bottomTags={{
          tags: dTranslatePartners(room.partners),
        }}
        crudOptions={{
          entity: translate("entities.broadcastRooms.broadcastRoomCrud"),
          entityName: dTranslate(room.title),
          onEdit: () => handleEditBroadcastRoom(id),
          onDelete: () => handleDeleteBroadcastRoom(id, category),
        }}
        onLive={onLive}
      />
    );
  };

  const renderFirstCell = category => {
    return (
      <AddButton
        title={translate("entities.broadcastRooms.addRoomButton")}
        placeholder={translate("entities.broadcastRooms.addRoomButton")}
        onClick={() => handleCreateBroadcastRoom(Number(category.id))}
      />
    );
  };

  const renderHeaderGroup = category => {
    return (
      <>
        <CategoryHeader>
          <Icon iconName="drag" />
          <Typography
            className="subTitle accent bold"
            style={{
              margin: "0 5px",
            }}
          >
            {dTranslate(category.name)}
          </Typography>

          <IconButton
            title={translate("entities.broadcastRooms.iconButtonTitle")}
            onClick={() => onCellEditClick(category)}
          >
            <Icon iconName="edit" size={16} variant="accent" />
          </IconButton>
        </CategoryHeader>
      </>
    );
  };

  return (
    <Container>
      {isLoaded ? (
        <>
          {categoriesList.length === 0 ? (
            <Empty
              style={{ position: "relative", bottom: 100 }}
              icon={<Icon iconName="broadcastRooms" />}
              message={translate(
                "entities.broadcastRooms.emptyCategories.message"
              )}
              description={translate(
                "entities.broadcastRooms.emptyCategories.description"
              )}
              button={{
                onClick: handleCreateCategories,
                children: translate("entities.categories.addCategories"),
                icon: <Icon variant="white" iconName="add" />,
              }}
              size="large"
            />
          ) : (
            <>
              <Header>
                <AddCategoryButton
                  type="button"
                  onClick={handleCreateCategories}
                >
                  {translate("entities.categories.addCategories")}
                </AddCategoryButton>
                <SearchBar
                  isSearching={isSearching}
                  placeholder={translate("entities.broadcastRooms.searchRoom")}
                  onChange={value => setSearch(value)}
                />
              </Header>
              <DraggableBoard
                cellGap={16}
                groupGap={32}
                data={categoriesList}
                renderCell={renderCell}
                renderFirstCell={renderFirstCell}
                renderHeaderGroup={renderHeaderGroup}
                onMoveItemOfColumn={(newOrderCategory, categoryIndex) => {
                  handleMoveItemOfColumn(newOrderCategory, categoryIndex);
                }}
                onDragItemToColumn={(
                  broadcastRoom,
                  categoryId,
                  categoryDestinationIndex,
                  categorySourceIndex
                ) => {
                  handleDragItemToAnotherColumn(
                    broadcastRoom,
                    categoryId,
                    categoryDestinationIndex,
                    categorySourceIndex
                  );
                }}
                onDragColumn={newOrderCategoires =>
                  handleReorderCategories(newOrderCategoires)
                }
              />
            </>
          )}
        </>
      ) : (
        <LoaderContainer gap={20} size={9} cellLoader={<CellLoader />} />
      )}
      <CategoryModal
        show={showModal}
        onClose={closeModal}
        type={CATEGORY_TYPES.BROADCAST_ROOM}
        orderLimit={categoriesList.length}
        onCreate={handleCreateCategory}
        onUpdate={handleUpdateCategory}
        onDelete={handleDeleteCategory}
        category={categoryData}
      />
    </Container>
  );
}

const BroadcastRoomCategoriesList = () => {
  return (
    <BroadcastRoomsProvider>
      <Content />
    </BroadcastRoomsProvider>
  );
};
export default BroadcastRoomCategoriesList;
