import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { Icon } from "../../atoms";
import { DialogModal as DeleteModal, SimpleMenu } from "../../molecules";
import { useTranslation } from "../../../hooks/translation";

const crudOptionsMenuPropTypes = {
  /** The name of the entity that will undergo the changes
   *
   * Example: An entity is <b>user</b>
   */
  entity: PropTypes.string.isRequired,

  /** The specific name of the entity that will undergo the changes
   *
   * Example: An entityName is <b>João</b>, that is the name of the <b>user</b>
   */
  entityName: PropTypes.string.isRequired,

  /** Callback that is triggered when user clicks into <b>edit</b> option, the return is <b>void</b> */
  onEdit: PropTypes.func,

  /** Callback that is triggered when user clicks into <b>delete</b> option, the return is <b>void</b> */
  onDelete: PropTypes.func,

  /** If <b>TRUE</b>, remove the <b>edit</b> option. Default is <b>false</b>. */
  onlyDelete: PropTypes.bool,

  /** If <b>TRUE</b>, remove the <b>delete</b> option. Default is <b>false</b>. */
  onlyEdit: PropTypes.bool,

  /** If this props has some element, override the default <b>IconButton</b>  */
  icon: PropTypes.element,

  /** Controls when to show the <b>Spinner</b> inside the <b>DeleteButton</b> at
   * the<b>DeleteModal</b>.
   */
  isDeleting: PropTypes.bool,
};

const crudOptionsMenuDefaultProps = {
  onEdit: () => {},
  onDelete: () => {},
  icon: null,
  onlyDelete: false,
  onlyEdit: false,
  isDeleting: false,
};

/** This Component is commonly used when you need an <b>SimpleMenu<b/> with,
 * the CRUD options, like "edti" and "delete" options.
 *
 * This components shows an <b>DialogModal</b> when the delete option is clicked.
 */
function CRUDOptionsMenu({
  entity,
  entityName,
  onEdit,
  onDelete,
  onlyDelete,
  onlyEdit,
  isDeleting,
  icon,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);

  // STATES
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  const items = [
    {
      id: 1,
      name: `${translate("common.edit")} ${entity}`,
      function: onEdit,
      icon: <Icon iconName="edit" />,
    },
    {
      id: 2,
      name: `${translate("common.delete")} ${entity}`,
      function: () => setShowDeleteModal(true),
      color: colors.redWrong,
      icon: <Icon iconName="delete" />,
    },
  ];

  if (onlyDelete) items.splice(0, 1);
  if (onlyEdit) items.splice(1, 1);

  return (
    <>
      <SimpleMenu
        fillItems={() => items}
        buttonIcon={icon || <Icon iconName="menu" />}
        {...rest}
      />

      <DeleteModal
        variant="error"
        style={{ minWidth: 400 }}
        isLoading={isDeleting}
        show={showDeleteModal}
        title={`${translate("common.delete")} ${entity} `}
        description={
          <>
            {translate("common.AreYouSureYouWantToDelete")}
            <strong>{entityName}</strong>?
          </>
        }
        onCancel={() => setShowDeleteModal(false)}
        onClose={() => setShowDeleteModal(false)}
        onConfirm={() => {
          setShowDeleteModal(false);

          onDelete();
        }}
      />
    </>
  );
}

CRUDOptionsMenu.propTypes = crudOptionsMenuPropTypes;
CRUDOptionsMenu.defaultProps = crudOptionsMenuDefaultProps;

export {
  CRUDOptionsMenu,
  crudOptionsMenuDefaultProps,
  crudOptionsMenuPropTypes,
};
