import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { EXTRA_EMBED_TYPES } from "~/shared/utils/types";
import { REF_TYPES } from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";
import { ExtraButtonList } from "~/modules/signed/modules/cms/components/lists/ExtraButtonList";
import { ExtraEmbedList } from "~/modules/signed/modules/cms/components/lists/ExtraEmbedList";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import { ScrollContainer } from "./styles";

export default function ExtraContentTab({ broadcastRoom }) {
  const { translate } = useTranslation();

  return (
    <ScrollContainer>
      <ExtraButtonList
        title={translate("entities.broadcastRooms.buttonsTabTitle")}
        buttonLabel={translate("entities.broadcastRooms.buttonsTabButtonLabel")}
        refId={broadcastRoom ? broadcastRoom.id : null}
        refType={REF_TYPES.BROADCAST_EXTRA_BUTTON}
      />
      <ExtraEmbedList
        title={translate("entities.broadcastRooms.embedTitle")}
        buttonLabel={translate("entities.broadcastRooms.embedAddLabel")}
        refId={isDataValid(broadcastRoom) ? broadcastRoom.id : null}
        refType={EXTRA_EMBED_TYPES.BROADCAST_ROOM}
        style={{ marginLeft: "20%" }}
      />
    </ScrollContainer>
  );
}

ExtraContentTab.propTypes = {
  broadcastRoom: PropTypes.object.isRequired,
};
