import React from "react";
import PropTypes from "prop-types";
import { useTranslation } from "~/shared/hooks/translation";

//* ASSETS
import goldFrame from "~/shared/assets/static/goldFrame.png";
import silverFrame from "~/shared/assets/static/silverFrame.png";
import bronzeFrame from "~/shared/assets/static/bronzeFrame.png";

//* APIs
import { resetScore as callResetScore } from "../../../../apis/gamification";

//* CUSTOM IMPORTS
import { Button } from "~/shared/components/atoms";
import { useAdminGamification } from "../../hooks";
import { AdminLabeledAvatarInput } from "../../../../../../components/AdminLabeledAvatarInput";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useAlert } from "~/shared/hooks/alert";
import { AdminRankingTable } from "../AdminRankingTable";
import {
  Form,
  LeftContainer,
  RightContainer,
  NumericInput,
  Container,
} from "./styles";
import { API_STATUS } from "~/shared/utils/apis";

const FrameInput = ({ color, ...rest }) => {
  const { translate } = useTranslation();

  return (
    <AdminLabeledAvatarInput
      {...rest}
      size={40}
      heigth={80}
      width={70}
      labelMessage={
        <>
          <strong>
            {translate("common.sendNewFrame")} {color}
          </strong>
          <br />
          Formatos aceitos (JPG | PNG)
        </>
      }
    />
  );
};

FrameInput.propTypes = {
  color: PropTypes.string.isRequired,
};

const Ranking = () => {
  const { translate } = useTranslation();
  const {
    saveRankingSettings,
    rankingSettings,
    rankingApiStatus,
  } = useAdminGamification();
  const { showFooterButtons, closeFooterButtons } = useCMS();
  const { showAlert } = useAlert();
  const { gold_frame, silver_frame, bronze_frame } = rankingSettings;

  //* STATES
  const [isReseting, setIsResetings] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => {
    showFooterButtons({
      disableCancelButton: true,
      formId: "admin-gamification-ranking-settings-form",
      isLoading: rankingApiStatus === API_STATUS.PENDING,
    });
  }, [rankingApiStatus, showFooterButtons]);

  React.useEffect(() => {
    return () => {
      closeFooterButtons();
    };
  }, [closeFooterButtons]);

  const handleOnSubmit = data => {
    showAlert({
      title: translate("entities.gamification.rankingSettings.confirmSave"),
      description: translate(
        "entities.gamification.rankingSettings.confirmSaveDescription"
      ),
      onConfirm: () => saveRankingSettings(data),
    });
  };

  const resetScore = async () => {
    setIsResetings(true);

    await callResetScore();

    setIsResetings(false);
  };

  const handleOnResetScore = () => {
    showAlert({
      title: "Zerar a pontuação de todos os participantes",
      description: (
        <>
          Você tem certeza de que deseja zerar a pontuação de todos os
          participante?<strong>Essa ação não pode ser desfeita</strong>
        </>
      ),
      confirmButton: "Zerar",
      variant: "error",
      onConfirm: resetScore,
      confirmSecurity: true,
    });
  };

  return (
    <Container>
      <LeftContainer>
        <Form
          onSubmit={handleOnSubmit}
          initialData={rankingSettings}
          id="admin-gamification-ranking-settings-form"
        >
          <Button
            style={{ marginBottom: 24 }}
            onClick={handleOnResetScore}
            loading={isReseting}
          >
            {translate("entities.gamification.reset")}
          </Button>

          <NumericInput
            title={translate("entities.gamification.usersOnGold")}
            tip={translate("components.input.usersOnGold.tip")}
            tipPosition="bottom"
            name="users_on_gold"
          />

          <FrameInput
            name="gold_frame"
            color={translate("common.gold")}
            initialValue={gold_frame || goldFrame}
          />

          <NumericInput
            title={translate("entities.gamification.usersOnSilver")}
            tip={translate("components.input.usersOnSilver.tip")}
            tipPosition="bottom"
            name="users_on_silver"
          />

          <FrameInput
            name="silver_frame"
            color={translate("common.silver")}
            initialValue={silver_frame || silverFrame}
          />

          <NumericInput
            title={translate("entities.gamification.usersOnBronze")}
            tip={translate("components.input.usersOnBronze.tip")}
            tipPosition="bottom"
            name="users_on_bronze"
          />

          <FrameInput
            name="bronze_frame"
            color={translate("common.bronze")}
            initialValue={bronze_frame || bronzeFrame}
          />
        </Form>
      </LeftContainer>

      <RightContainer>
        <AdminRankingTable />
      </RightContainer>
    </Container>
  );
};

export { Ranking };
