import produce from "immer";
import { isDataValid } from "~/shared/utils/validations";

const INITIAL_STATE = {
  roomSchedule: {
    data: [],
    isLoaded: false,
  },
};

export default function schedulesReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // FETCH ROOM SCHEDULE
      case "@schedules/FETCH_ROOM_SCHEDULE_SUCCESSFUL": {
        const { roomSchedule } = action.payload;

        if (isDataValid(roomSchedule)) draft.roomSchedule.data = roomSchedule;

        draft.roomSchedule.isLoaded = true;
        break;
      }

      case "@schedules/CLEAR_ROOM_SCHEDULE": {
        draft.roomSchedule.data = [];
        draft.roomSchedule.isLoaded = false;
        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        break;
      }
      default:
    }
  });
}
