import styled, { css } from "styled-components";

export const Paragraph = styled.p`
  margin: 0;

  ${props => {
    if (props.maxLines)
      return css`
        word-break: break-word;

        @supports (-webkit-line-clamp: ${props.maxLines}) {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: initial;
          display: -webkit-box;
          -webkit-line-clamp: ${props.maxLines};
          -webkit-box-orient: vertical;
        }
      `;
  }}

  &.hoverable {
    &:hover {
      text-decoration: underline;
    }
  }

  &.wordBreak {
    word-break: break-all;
  }

  &.italic {
    font-style: italic;
  }

  &.capitalize {
    text-transform: capitalize;
  }

  &.centered {
    text-align: center;
  }

  &.left {
    text-align: left;
  }

  &.uppercase {
    text-transform: uppercase;
  }

  //* font-weight
  &.lighter {
    font-weight: lighter;
  }

  &.bold {
    font-weight: bold;
  }

  //* FONT SIZES
  &.bigTitle {
    font-family: "Montserrat", sans-serif;
    font-size: 28px;
  }

  &.title {
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
  }

  &.tinyTitle {
    font-family: "Montserrat", sans-serif;
    font-size: 20px;
  }

  &.subTitle {
    font-size: 16px;
  }

  &.regular {
    font-size: 14px;
  }

  &.small {
    font-size: 12px;
  }

  &.tiny {
    font-size: 10px;
  }

  //* COLORS
  &.strong {
    color: ${props => props.theme.colors.strong};
  }

  &.normal {
    color: ${props => props.theme.colors.normal};
  }

  &.opaque {
    color: ${props => props.theme.colors.opaque};
  }

  &.smoked {
    color: ${props => props.theme.colors.smoked};
  }

  &.accent {
    color: ${props => props.theme.colors.accent};
  }

  &.white {
    color: ${props => props.theme.colors.surface};
  }

  &.opaqueWhite {
    color: "#ffffff50";
  }

  &.error {
    color: ${props => props.theme.colors.redWrong};
  }
`;
