import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";
import IntlTelInput from "react-intl-tel-input";
import ReactInputMask from "react-input-mask";

//* CUSTOM IMPORTS
import { isStringEmpty } from "../../../utils/strings";
import { InputContainer } from "..";
import { Icon } from "../Icon";
import { Container } from "./styles";

const phoneInputPropTypes = {
  /** Name is required to reference the input inside the form  */
  name: PropTypes.string.isRequired,

  /** Add an title above input */
  title: PropTypes.string,

  className: PropTypes.string,

  /** Add an icon in the left, the icon is the <b>iconName</b> of the
   * <b>Atoms/Icon</b> component.
   */
  icon: PropTypes.string,

  /** Render an loader instead of the input */
  isLoading: PropTypes.bool,

  /** Add an <b>*</b> in the input title */
  isRequired: PropTypes.bool,

  style: PropTypes.object,

  /** If index exists, will set an zIndex to control overflow layer, when
   * form has many input one above another
   */
  index: PropTypes.number,
};

const phoneInputDefaultProps = {
  title: null,
  className: null,
  icon: null,
  isLoading: false,
  isRequired: false,
  style: null,
  index: null,
};

/** The PhoneInput component use <b>Form</b> component to anotate the current value, so he
 *  needs to be inside an <b>Form</b>. */
function PhoneInput({
  name,
  title,
  className,
  icon,
  isRequired,
  isLoading,
  style,
  index,
  ...rest
}) {
  const dialCodeInputName = `${name}-dialCode`;
  const countryInputName = `${name}-country`;

  //* REFS
  const inputRef = React.useRef(null);
  const dialCodeRef = React.useRef(null);
  const countryRef = React.useRef(null);

  const { fieldName, defaultValue, error, registerField } = useField(name);
  const dialCode = useField(dialCodeInputName);
  const country = useField(countryInputName);

  //* STATES
  const [isFocused, setIsFocused] = React.useState(false);
  const [isFilled, setIsFilled] = React.useState(defaultValue || false);
  const [dialCodeValue, setDialCodeValue] = React.useState(
    dialCode.defaultValue || "55"
  );
  const [countryValue, setCountryValue] = React.useState(
    country.defaultValue || "br"
  );
  const [isRemounting, setIsRemounting] = React.useState(false);
  const [inputValue, setInputValue] = React.useState(defaultValue);

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, registerField]);

  React.useEffect(() => {
    dialCode.registerField({
      name: dialCode.fieldName,
      ref: dialCodeRef.current,
      path: "value",
    });
  }, [dialCode, dialCode.registerField]);

  React.useEffect(() => {
    country.registerField({
      name: country.fieldName,
      ref: countryRef.current,
      path: "value",
    });
  }, [country, country.registerField]);

  const renderPhoneMask = () => {
    const intTelInput = document.querySelector(
      ".intl-tel-input.allow-dropdown input"
    );

    if (intTelInput)
      return String(intTelInput.placeholder).replace(/[0-9]/g, "9");

    return null;
  };

  const renderInputContainerVariant = () => {
    if (isFocused) return "focus";

    if (error) return "error";

    if (isFilled) return "filled";

    return null;
  };

  React.useEffect(() => {
    if (country.defaultValue) {
      setCountryValue(country.defaultValue);
      setIsRemounting(true);

      setTimeout(() => setIsRemounting(false), 1);
    }
  }, [country.defaultValue]);

  return (
    <Container className={className} style={style} index={100 - index}>
      <InputContainer
        isLoading={isLoading}
        title={title}
        isRequired={isRequired}
        variant={renderInputContainerVariant()}
        onFocus={() => setIsFocused(true)}
        onBlur={() => {
          setIsFocused(false);
          setIsFilled(!isStringEmpty(inputRef.current.value));
        }}
        icon={<Icon iconName={icon} />}
        errorMessage={error}
      >
        <>
          <input
            ref={dialCodeRef}
            name={dialCodeInputName}
            value={dialCodeValue}
            defaultValue={dialCode.defaultValue}
            style={{ display: "none" }}
          />
          <input
            ref={countryRef}
            name={countryInputName}
            value={countryValue}
            defaultValue={country.defaultValue}
            style={{ display: "none" }}
          />

          {!isRemounting && (
            <IntlTelInput
              id={`${name}-tel-input`}
              preferredCountries={["br", "us", "es"]}
              defaultCountry={countryValue}
              onSelectFlag={(_, c) => {
                const { dialCode: code, iso2: countryName } = c;

                setDialCodeValue(code);
                setCountryValue(countryName);
                setInputValue("");
              }}
              style={{ marginRight: 50 }}
            />
          )}

          <ReactInputMask
            {...rest}
            inputMode="numeric"
            mask={renderPhoneMask()}
            inputRef={ref => {
              inputRef.current = ref;
            }}
            onChange={e => setInputValue(e.target.value)}
            value={inputValue}
          />
        </>
      </InputContainer>
    </Container>
  );
}

PhoneInput.propTypes = phoneInputPropTypes;

PhoneInput.defaultProps = phoneInputDefaultProps;

export { PhoneInput, phoneInputPropTypes, phoneInputDefaultProps };
