import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listExtraPlayers,
  deleteExtraPlayer,
  clearExtraPlayers,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

//* CUSTOM IMPORTS
import Loader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { openExtraPlayerModal } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { IconedButton, Box, Typography } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import { ExtraPlayerCell } from "~/shared/components/organisms/cells/ExtraPlayerCell";
import { useTranslation } from "~/shared/hooks/translation";

const extraPlayerListPropTypes = {
  title: PropTypes.string.isRequired,

  buttonLabel: PropTypes.string.isRequired,

  /** The room ID the extra button belongs to */
  refId: PropTypes.number.isRequired,

  /** The type of button that will be created, you can acess all the REF_TYPES
   * in <b>modules/signed/modules/admin/store/extraLinks/action</b>.
   */
  refType: PropTypes.string.isRequired,
};

const ExtraPlayerList = ({ title, buttonLabel, refId, refType, ...rest }) => {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: extraPlayers, isLoaded, isDeleting, isUpdating } = useSelector(
    state => state.adminExtraLinks.extraPlayers
  );

  //* STATES
  const [loadingId, setLoadingId] = React.useState(null);

  //* FUNCTIONS
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(refId))
      dispatch(listExtraPlayers(refId, refType));

    return () => {
      dispatch(clearExtraPlayers());
      isMounted = false;
    };
  }, [dispatch, refId, refType]);

  const renderCell = extraPlayer => {
    const { id, url, videoType, label, type } = extraPlayer;

    return (
      <ExtraPlayerCell
        title={dTranslate(label)}
        iconName={type}
        videoPlayer={{ src: url, type: videoType }}
        onDelete={() => {
          setLoadingId(id);
          dispatch(deleteExtraPlayer(id));
        }}
        onEdit={() => {
          setLoadingId(id);
          openExtraPlayerModal(refType, refId, extraPlayer);
        }}
        entity={translate("entities.broadcastRooms.accessibilityTabTitle")}
      />
    );
  };

  return (
    <Box {...rest}>
      <Typography
        className="normal small uppercase bold"
        style={{ marginBottom: 10 }}
      >
        {title}
      </Typography>

      <IconedButton
        type="button"
        onClick={() => openExtraPlayerModal(refType, refId)}
      >
        {buttonLabel}
      </IconedButton>

      <CellList
        cellId={loadingId}
        isUpdating={isUpdating === "PENDING"}
        isDeleting={isDeleting === "PENDING"}
        isLoaded={!refId ? true : isLoaded}
        data={extraPlayers}
        renderCell={renderCell}
        gap={20}
        renderCellLoader={() => <Loader />}
        containerStyle={{ marginTop: 20 }}
        emptyComponent={
          <Empty
            message={translate("entities.extraBroadcastRooms.empty.message")}
            description={translate(
              "entities.extraBroadcastRooms.empty.description"
            )}
            style={{ alignItems: "flex-start" }}
          />
        }
      />
    </Box>
  );
};

ExtraPlayerList.propTypes = extraPlayerListPropTypes;

export { ExtraPlayerList };
