import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import logo from "../../../assets/custom/app_white_logo@2x.png";
import { ButtonGroup, Infos } from "../../molecules";
import { avatarPropTypes, Icon, HoverWrapper, Button } from "../../atoms";
import { Container, SlapHole, LeftBox, RightBox, Logo, Title } from "./styles";

const homeHeaderPropTypes = {
  variant: PropTypes.oneOf(["default", "return"]),
  /** An array of <b>Button</b>, if this array is not empty will render an
   * <b>ButtonGroup</b> in the left of Header.
   */
  leftNavigation: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      pathname: PropTypes.string,
      name: PropTypes.string,
      icon: PropTypes.element,
      onClick: PropTypes.func,
    })
  ),
  /** Any element to be rendered at right of this component. */
  rightComponent: PropTypes.element,
  /** Control all <b>Avatar</b> props. See all props into <b>Atoms/Avatar</b>. */
  avatar: PropTypes.shape(avatarPropTypes),
  /** Callback when user clicks into <b>Avatar,/b> component. */
  onAvatarClick: PropTypes.func,
  /** Callback when user clicks into <b>Logo</b>. */
  onLogoClick: PropTypes.func,
  /** Callback when user clicks into <b>LeftButton</b> */
  onLeftButtonClick: PropTypes.func,
  /** An Tip for the default left button */
  leftButtonTip: PropTypes.string,
  /** Render an tiutle at left of <b>Logo</b> component. */
  title: PropTypes.string,
  /** Callback when user clicks into some item inside the leftNavigation or rightNavigation.
   * Return the item clicked.
   *
   * Example: (item) => {}
   */
  onItemClick: PropTypes.func,
  /** The userName that will displayed at right of <b>Avatar</b>. */
  userName: PropTypes.string,
};

const homeHeaderDefaultProps = {
  avatar: null,
  variant: "default",
  leftNavigation: [],
  onLeftButtonClick: () => {},
  onAvatarClick: () => {},
  onLogoClick: () => {},
  onItemClick: () => {},
  rightComponent: null,
  leftButtonTip: null,
  userName: null,
  title: null,
};

/** The Default Header for application */
const Header = ({
  avatar,
  variant,
  title,
  leftNavigation,
  rightComponent,
  onAvatarClick,
  onLogoClick,
  onLeftButtonClick,
  leftButtonTip,
  onItemClick,
  userName,
  ...rest
}) => {
  //* FUNCTIONS
  const renderButtonGroup = navigation =>
    navigation.map(nav => {
      if (nav.isSelected) return nav;

      //* *Remove button children of button that is not selected */
      return { ...nav, children: null };
    });

  return (
    <>
      <Container {...rest} variant={variant}>
        <LeftBox>
          <Button
            forceTip
            className="header-left-button"
            variant="primary"
            isSelected={variant === "return"}
            tip={leftButtonTip}
            onClick={onLeftButtonClick}
          >
            <Icon
              iconName={variant === "return" ? "arrowBack" : "hamburguerMenu"}
            />
          </Button>

          {title && (
            <Title
              className="white subTitle bold header-title"
              style={{ marginLeft: 16 }}
            >
              {title}
            </Title>
          )}

          <Logo className="header-logo" src={logo} onClick={onLogoClick} />

          {leftNavigation.length > 0 && (
            <ButtonGroup
              animated
              className="header-left-buttons"
              buttons={renderButtonGroup(leftNavigation)}
              variant="primary"
              orientation="row"
              marginHorizontal={10}
              onButtonClick={button => onItemClick(button)}
            />
          )}
        </LeftBox>

        <RightBox>
          {rightComponent}

          {avatar && (
            <HoverWrapper
              onClick={onAvatarClick}
              style={{ marginLeft: 8 }}
              className="header-user-infos"
            >
              <Infos
                avatar={avatar}
                title={userName}
                titleStyles="white bold"
              />
            </HoverWrapper>
          )}
        </RightBox>
      </Container>

      <SlapHole />
    </>
  );
};

Header.propTypes = homeHeaderPropTypes;
Header.defaultProps = homeHeaderDefaultProps;

export { Header, homeHeaderPropTypes, homeHeaderDefaultProps };
