import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Button, Typography, Line } from "../../atoms";
import { CellList } from "../../molecules";
import { Container, Header } from "./styles";

const sideMenuPropTypes = {
  /** An array of arrays to be rendered at top of this component. */
  topNavigation: PropTypes.arrayOf([
    PropTypes.shape({
      id: PropTypes.id,
      title: PropTypes.string,
      data: PropTypes.arrayOf(
        PropTypes.shape({
          id: PropTypes.id,
          content: PropTypes.string,
          icon: PropTypes.element,
        })
      ),
    }),
  ]).isRequired,
  /** An array of items to be rendered at bottom of this component. */
  bottomNavigation: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.id,
      content: PropTypes.string,
      icon: PropTypes.element,
    })
  ),
  /** Any element to be rendered at top of this component. */
  headerComponent: PropTypes.element,
  /** Callback when users clicks into an item. Return the item clicked. */
  onItemClick: PropTypes.func,
  /** The id for the selected item in SideMenu */
  selectedItem: PropTypes.number,
  /** Callback that return the scrollRef */
  scrollRef: PropTypes.func,
  /** The variants for this component, default variant is <b>surface</b> */
  variant: PropTypes.oneOf(["primary", "surface"]),
};

const sideMenuDefaultProps = {
  bottomNavigation: [],
  headerComponent: null,
  onItemClick: () => {},
  selectedItem: null,
  scrollRef: () => [],
  variant: "surface",
};

const SideMenu = ({
  onItemClick,
  topNavigation,
  bottomNavigation,
  headerComponent,
  selectedItem,
  scrollRef,
  variant,
  ...rest
}) => {
  const maxBottomNavHeight = bottomNavigation.length * 70;
  const maxHeaderComponentHeight = 120;
  const maxTopNavHeight = `calc(100vh - ${maxBottomNavHeight +
    maxHeaderComponentHeight}px)`;

  const buttonVariant = React.useMemo(() => {
    switch (variant) {
      case "primary":
        return "primary";

      default:
        return "text";
    }
  }, [variant]);

  return (
    <Container {...rest} variant={variant}>
      {headerComponent && <Header>{headerComponent}</Header>}

      <CellList
        isLoaded
        withScroll
        containerRef={scrollRef}
        className="side-menu-top-nav"
        noScrollReposition
        scrollStyle={{
          padding: "0 20px 20px",
          maxHeight: maxTopNavHeight,
        }}
        orientation="column"
        gap={30}
        data={topNavigation}
        renderCell={navigation => (
          <CellList
            isLoaded
            gap={8}
            headerComponent={
              <Typography
                className="bold uppercase small normal side-menu-text"
                style={{ marginBottom: 8 }}
              >
                {navigation.title}
              </Typography>
            }
            orientation="column"
            data={navigation.data}
            renderCell={item => {
              const { content, icon, id } = item;

              return (
                <Button
                  onClick={() => onItemClick(item)}
                  variant={buttonVariant}
                  icon={icon}
                  isSelected={selectedItem === id}
                >
                  {content}
                </Button>
              );
            }}
          />
        )}
      />

      <Line
        style={{
          height: 3,
          width: "calc(100% - 40px)",
          margin: "0 auto 20px",
        }}
      />

      {bottomNavigation.length > 0 && (
        <CellList
          isLoaded
          orientation="column"
          data={bottomNavigation}
          listStyle={{
            padding: "0 20px",
            maxHeight: maxBottomNavHeight,
            marginBottom: 16,
          }}
          renderCell={item => (
            <Button
              onClick={() => onItemClick(item)}
              variant={buttonVariant}
              icon={item.icon}
              isSelected={selectedItem === item.id}
            >
              {item.content}
            </Button>
          )}
        />
      )}
    </Container>
  );
};

SideMenu.propTypes = sideMenuPropTypes;

SideMenu.defaultProps = sideMenuDefaultProps;

export { SideMenu, sideMenuPropTypes, sideMenuDefaultProps };
