/* eslint-disable react/prop-types */
import React from "react";

//* CUSTOM IMPORTS
import { Avatar, Typography } from "../../../../../atoms";
import {
  Touchable,
  ContentWrapper,
  Content,
  Banner,
  Tags,
  CRUDOptionsMenu,
  CustomButtonContent,
} from "./styles";

/** DiamondPartnerCell is an variant of PartnerCell, to see all props see the
 * PartnerCell
 */
const DiamondPartnerCell = ({
  banner,
  avatar,
  title,
  subTitle,
  tags,
  bio,
  crudOptions,
  customButton,
  onClick,
  ...rest
}) => (
  <Touchable {...rest}>
    <ContentWrapper onClick={onClick}>
      <Banner src={banner} className="diamond-sponsor-banner" />

      <Content className="diamond-sponsor-content">
        <Avatar src={avatar} size={56} style={{ marginRight: 8 }} />

        <div className="diamond-sponsor-infos">
          <Typography maxLines={1} className="subTitle normal bold">
            {title}
          </Typography>

          {subTitle && (
            <Typography maxLines={1} className="opaque bold small">
              {subTitle}
            </Typography>
          )}

          <Tags tags={tags} trim />

          {bio && (
            <Typography
              className="opaque small"
              maxLines={2}
              style={{ marginTop: 8 }}
            >
              {bio}
            </Typography>
          )}
        </div>
      </Content>
    </ContentWrapper>

    {crudOptions && (
      <CRUDOptionsMenu
        {...crudOptions}
        className="diamond-sponsor-admin-options"
        entityName={title}
      />
    )}

    {customButton && (
      <CustomButtonContent className="diamond-sponsor-custom-button">
        {customButton}
      </CustomButtonContent>
    )}
  </Touchable>
);

export { DiamondPartnerCell };
