import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, Progress } from "./styles";

const progressBarPropTypes = {
  /** Control the animation time in seconds */
  animationTime: PropTypes.number,

  /** Control the animation time in seconds */
  animationStatus: PropTypes.oneOf(["paused", "runnig"]),
};

const progressBarDefaultProps = {
  animationTime: 5,
  animationStatus: "runnig",
};

/** Used for progress feedback */
function ProgressBar({ animationTime, animationStatus, ...rest }) {
  return (
    <Container {...rest}>
      <Progress
        className="progressBar"
        animationTime={animationTime}
        animationStatus={animationStatus}
      />
    </Container>
  );
}

ProgressBar.propTypes = progressBarPropTypes;

ProgressBar.defaultProps = progressBarDefaultProps;

export { ProgressBar, progressBarPropTypes, progressBarDefaultProps };
