import React from "react";
import { useSelector, useDispatch } from "react-redux";

// APIs
import {
  listCertificates,
  deleteCertificate,
} from "~/modules/signed/modules/cms/modules/admin/store/certificates/actions";

// CUSTOM IMPORTS
import { CellList, Empty } from "~/shared/components/molecules";
import { NetworkRoomCell as CertificateCell } from "~/shared/components/organisms/cells/NetworkRoomCell";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { OutlinedButton, Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

import {
  openCreateCertificate,
  openEditCertificate,
} from "~/shared/routes/navigation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function Certificates() {
  const dispatch = useDispatch();
  const {
    data: certificates,
    isLoaded,
    isDeleting,
    certificateId,
  } = useSelector(state => state.adminCertificates.list);

  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    dispatch(listCertificates());
  }, [dispatch, isLoaded]);

  const handleDeleteCell = id => {
    dispatch(deleteCertificate({ certificateId: id }));
  };

  const renderCell = certificate => {
    const { id, banner, description, title } = certificate;

    return (
      <div style={{ display: "flex" }}>
        {certificateId === certificate.id && isDeleting === "PENDING" ? (
          <CellLoader isDeleting={isDeleting === "PENDING"} />
        ) : (
          <CertificateCell
            isAdmin
            image={banner}
            title={dTranslate(title)}
            description={dTranslate(description)}
            onClick={() => openEditCertificate({ certificateId: id })}
            crudOptionsModal={{
              entity: translate("entities.certificates.singular"),
              entityName: dTranslate(title),
              onDelete: () => handleDeleteCell(id),
              onEdit: () => openEditCertificate({ certificateId: id }),
            }}
          />
        )}
      </div>
    );
  };

  const isEmpty = isLoaded && certificates.length === 0;

  return (
    <CellList
      isLoaded={isLoaded}
      data={certificates}
      renderCell={renderCell}
      containerStyle={{ height: "100%" }}
      listStyle={{ height: isEmpty ? "100%" : "unset" }}
      renderCellLoader={() => (
        <CellLoader
          style={{ height: 230, width: 300 }}
          height={230}
          width={300}
        />
      )}
      gap={20}
      loaderListSize={9}
      firstCell={
        !isEmpty && (
          <OutlinedButton
            onClick={openCreateCertificate}
            placeholder={translate("entities.certificates.add")}
            style={{ width: 300 }}
          />
        )
      }
      emptyComponent={
        <Empty
          style={{ position: "relative", bottom: 100 }}
          icon={<Icon iconName="certificates" />}
          message={translate("entities.certificates.empty.message")}
          description={translate("entities.certificates.empty.description")}
          button={{
            onClick: openCreateCertificate,
            children: translate("entities.certificates.add"),
            icon: <Icon iconName="add" color="#fff" />,
          }}
        />
      }
    />
  );
}

export default Certificates;
