// This function return true if a string has at least one character
// If string just have white spaces will return as a empty string
export const isStringEmpty = string => {
  if (/\S/.test(string)) return false;

  return true;
};

export const toHtml = ({ string }) => {
  const htmlString = {
    __html: string,
  };

  return htmlString;
};

/** This function compare if the parameter is the same domain as the actual hostname */
export const isTheSameDomain = string =>
  String(string).includes(String(window.location.hostname));

export const capitalize = string => {
  if (typeof string !== "string") return "";
  return string.charAt(0).toUpperCase() + string.slice(1);
};

export const toSnakeCase = string =>
  String(string)
    .replace(" ", "_")
    .toLowerCase();

export const removeSnakeCase = string => String(string).replace("_", " ");
