import {
  addMinutes,
  getDayOfYear,
  getHours,
  getMinutes,
  isAfter,
  isBefore,
  setDayOfYear,
  setHours,
  setMinutes,
  subMinutes,
  isSameMinute,
} from "date-fns";

export const getAvailableHours = ({
  start_date,
  end_date,
  meeting_time,
  break_time,
  blocked_hours,
}) => {
  const day = new Date();
  const availableHours = [];

  const startHour = setDayOfYear(new Date(start_date), getDayOfYear(day));
  const endHour = setDayOfYear(new Date(end_date), getDayOfYear(day));

  for (
    let avDate = setMinutes(
      setHours(new Date(), getHours(startHour)),
      getMinutes(startHour)
    );
    isAfter(avDate, subMinutes(startHour, 1)) &&
    isBefore(avDate, addMinutes(endHour, 1));
    avDate = addMinutes(avDate, meeting_time + break_time)
  ) {
    const startDate = avDate;
    const endDate = addMinutes(avDate, meeting_time);
    const isBlocked = blocked_hours.some(blockeHour =>
      isSameMinute(blockeHour.start_date, startDate)
    );

    availableHours.push({
      startDate,
      endDate,
      isBlocked,
    });
  }

  return availableHours;
};
