import React from "react";

//* CUSTOM IMPORTS
import { Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { clientPaths } from "../../../../routes/paths";
import { useSigned } from "~/modules/signed/hooks";
import { useWindow } from "~/shared/hooks/window";
import { USER_TYPES } from "~/shared/utils/user";
import { useGamification } from "~/modules/signed/hooks/gamification";

export const USER_NAVIGATIONS = {
  BROADCAST_ROOMS: {
    id: 1,
    pathname: clientPaths.home.broadcastRooms.index,
    icon: <Icon iconName="broadcastRooms" />,
  },
  MARKET_ROOMS: {
    id: 2,
    pathname: clientPaths.home.marketRooms.index,
    icon: <Icon iconName="marketRooms" />,
  },
  NETWORK_ROOMS: {
    id: 3,
    pathname: clientPaths.home.networkRooms.index,
    icon: <Icon iconName="networkRooms" />,
  },
  POSTERS: {
    id: 4,
    pathname: clientPaths.home.posters.index,
    icon: <Icon iconName="posters" />,
  },
  SCHEDULE: {
    id: 5,
    pathname: clientPaths.home.schedule.index,
    icon: <Icon iconName="schedule" />,
  },
  EDIT_PROFILE: {
    id: 6,
    pathname: clientPaths.profile.index,
    icon: <Icon iconName="person" />,
  },
  CERTIFICATES: {
    id: 7,
    pathname: clientPaths.home.certificates.index,
    icon: <Icon iconName="certificates" />,
  },
  DONWLOAD_MATERIALS: {
    id: 8,
    pathname: clientPaths.home.donwloadMaterials.index,
    icon: <Icon iconName="download" />,
  },
  STICKY_NOTES: {
    id: 9,
    pathname: clientPaths.home.stickyNotes.index,
    icon: <Icon iconName="stickyNotes" />,
  },
  MY_SCHEDULE: {
    id: 10,
    pathname: clientPaths.mySchedule.index,
    icon: <Icon iconName="schedule" />,
  },
  GAMIFICATION: {
    id: 11,
    pathname: clientPaths.home.ranking.index,
    icon: <Icon iconName="trophy" />,
  },

  DASHBOARD: {
    id: 99,
    icon: <Icon iconName="dashboard" />,
  },
  EXIT: {
    id: 100,
    icon: <Icon iconName="exit" />,
  },
};

const {
  EXIT,
  POSTERS,
  SCHEDULE,
  DASHBOARD,
  MY_SCHEDULE,
  GAMIFICATION,
  CERTIFICATES,
  EDIT_PROFILE,
  MARKET_ROOMS,
  STICKY_NOTES,
  NETWORK_ROOMS,
  BROADCAST_ROOMS,
  DONWLOAD_MATERIALS,
} = USER_NAVIGATIONS;

const {
  REACT_APP_HAS_POSTERS: postersModule,
  REACT_APP_HAS_SCHEDULE: scheduleModule,
  REACT_APP_HAS_CERTIFICATE: certificatesModule,
  REACT_APP_HAS_MARKET_ROOMS: marketRoomModule,
  REACT_APP_HAS_STICKY_NOTES: stickyNotesModule,
  REACT_APP_HAS_NETWORK_ROOMS: networkRoomsModule,
  REACT_APP_HAS_DOWNLOAD_MATERIALS: downloadMaterialsModule,
  REACT_APP_HAS_GAMIFICATION: gamificationModule,
} = process.env;

const isModuleEnabled = module => module === "true";

const useClientHeader = () => {
  const { isMyUserAdmin, user } = useSigned();
  const { access_type: accessType, partners_ids: partnerIds } = user;
  const { translate } = useTranslation();
  const { isMediaLargeTablet } = useWindow();
  const { settings, isSettingsLoaded } = useGamification();

  //* STATES
  const [headerNavigation, setHeaderNavigation] = React.useState([]);
  const [drawerTopNavigation, setDrawerTopNavigation] = React.useState([]);
  const [drawerBottomNavigation, setDrawerBottomNavigation] = React.useState(
    []
  );

  React.useEffect(() => {
    const enabledModules = [
      {
        ...BROADCAST_ROOMS,
        tip: translate("entities.broadcastRooms.plural"),
        content: translate("entities.broadcastRooms.plural"),
      },
    ];

    if (isModuleEnabled(marketRoomModule))
      enabledModules.push({
        ...MARKET_ROOMS,
        tip: translate("entities.marketRooms.singular"),
        content: translate("entities.marketRooms.singular"),
      });

    if (isModuleEnabled(networkRoomsModule))
      enabledModules.push({
        ...NETWORK_ROOMS,
        tip: translate("entities.networkRooms.plural"),
        content: translate("entities.networkRooms.plural"),
      });

    if (isModuleEnabled(scheduleModule))
      enabledModules.push({
        ...SCHEDULE,
        tip: translate("entities.schedule.singular"),
        content: translate("entities.schedule.singular"),
      });

    setHeaderNavigation(enabledModules);
  }, [translate]);

  React.useEffect(() => {
    const enabledModules = [];

    if (isMediaLargeTablet) {
      enabledModules.push({
        ...BROADCAST_ROOMS,
        content: translate("entities.broadcastRooms.plural"),
      });

      if (isModuleEnabled(marketRoomModule))
        enabledModules.push({
          ...MARKET_ROOMS,

          content: translate("entities.marketRooms.singular"),
        });

      if (isModuleEnabled(networkRoomsModule))
        enabledModules.push({
          ...NETWORK_ROOMS,

          content: translate("entities.networkRooms.plural"),
        });

      if (isModuleEnabled(scheduleModule))
        enabledModules.push({
          ...SCHEDULE,
          content: translate("entities.schedule.singular"),
        });
    }

    if (isModuleEnabled(postersModule))
      enabledModules.push({
        ...POSTERS,
        content: translate("entities.posters.plural"),
      });

    if (isModuleEnabled(stickyNotesModule))
      enabledModules.push({
        ...STICKY_NOTES,
        content: translate("entities.stickyNotes.plural"),
      });

    if (isModuleEnabled(gamificationModule) && isSettingsLoaded) {
      if (settings.is_enabled && settings.show_user_ranking)
        enabledModules.push({
          ...GAMIFICATION,
          content: translate("common.ranking"),
        });
    }

    if (isModuleEnabled(certificatesModule))
      enabledModules.push({
        ...CERTIFICATES,
        content: translate("entities.certificates.myCertificates"),
      });

    if (isModuleEnabled(downloadMaterialsModule))
      enabledModules.push({
        ...DONWLOAD_MATERIALS,
        content: translate("entities.downloadMaterials.downloadableMaterials"),
      });

    enabledModules.push({
      ...MY_SCHEDULE,
      content: translate("entities.meetings.myMeetings"),
    });

    setDrawerTopNavigation(enabledModules);
  }, [isMediaLargeTablet, isSettingsLoaded, settings, translate]);

  React.useEffect(() => {
    const enabledModules = [
      { ...EDIT_PROFILE, content: translate("common.accessProfile") },
      { ...EXIT, content: translate("common.logOff") },
    ];

    const dashboard = {
      ...DASHBOARD,
      content: translate("common.dashboard"),
    };

    if (isMyUserAdmin) {
      /** If user accessTye is Sponsor and he do not has an viculated partner,
       * disable the dashboard module
       */
      if (accessType === USER_TYPES.SPONSOR.value) {
        if (partnerIds)
          if (partnerIds.length) enabledModules.unshift(dashboard);
      } else {
        /** Enable dashboard module if user is admin */
        enabledModules.unshift(dashboard);
      }
    }

    setDrawerBottomNavigation(enabledModules);
  }, [accessType, isMyUserAdmin, partnerIds, translate]);

  return { headerNavigation, drawerTopNavigation, drawerBottomNavigation };
};

export { useClientHeader };
