import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Typography, Icon, HoverWrapper } from "~/shared/components/atoms";
import { CellList } from "~/shared/components/molecules";
import { getDistanceToNow } from "~/shared/utils/dates";
import Loader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { useSigned } from "~/modules/signed/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { StickyNoteCell, OutlinedButton } from "./styles";

const stickyNotesPropTypes = {
  title: PropTypes.string.isRequired,

  /** This data accompany the API response */
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  /** Callback that is trigerred when user clicks into <b>AddButton</b>. */
  onAdd: PropTypes.func,

  /** Callback that is trigerred when user clicks to delete an <b>StickyNoteCell</b>. */
  onDelete: PropTypes.func,

  /** Callback that is trigerred when user clicks into <b>StickyNoteCell</b>. */
  onStickyNoteClick: PropTypes.func,
};

const stickyNotesDefaultProps = {
  onStickyNoteClick: () => {},
  onDelete: () => {},
  onAdd: () => {},
};

const StickyNotesList = ({
  title,
  data,
  onAdd,
  onDelete,
  onStickyNoteClick,
  ...rest
}) => {
  const { isMyUserId, isMyUserAdmin } = useSigned();
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();

  //* STATES
  const [random, setRandom] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    data.forEach(d => {
      setRandom(state => {
        const rotation = Math.random() * (9 - -9) + -9;

        return [...state, { id: d.id, rotation }];
      });
    });
  }, [data]);

  const getRotation = React.useCallback(
    id => {
      const index = random.findIndex(r => r.id === id);

      if (index !== -1) return random[index].rotation;

      return null;
    },
    [random]
  );

  const renderFoldPosition = React.useCallback(rotation => {
    if (rotation >= -9 && rotation <= -3) return "bottomLeft";

    if (rotation > -3 && rotation <= 3) return "topLeft";

    return "bottomRight";
  }, []);

  const enableDeleteStickyNote = React.useCallback(
    userId => {
      if (isMyUserAdmin) return true;

      if (isMyUserId(userId)) return true;

      return false;
    },
    [isMyUserAdmin, isMyUserId]
  );

  const renderCell = React.useCallback(
    stickyNote => {
      const { message, color, author, created_at: createdAt, id } = stickyNote;

      const rotation = getRotation(id);

      return (
        <HoverWrapper key={id}>
          <StickyNoteCell
            className="sticky-notes-cell"
            foldPosition={renderFoldPosition(rotation)}
            rotation={rotation}
            onClick={() => onStickyNoteClick(stickyNote)}
            avatar={{ src: author.avatar }}
            title={author.name}
            subTitle={getDistanceToNow(createdAt)}
            content={message}
            color={color}
            simpleMenu={
              enableDeleteStickyNote(author.id) && {
                fillItems: () => [
                  {
                    id: 1,
                    name: translate("entities.stickyNotes.delete"),
                    function: () => onDelete(stickyNote),
                    icon: <Icon iconName="delete" />,
                    color: colors.redWrong,
                  },
                ],
              }
            }
          />
        </HoverWrapper>
      );
    },
    [
      colors.redWrong,
      enableDeleteStickyNote,
      getRotation,
      onDelete,
      onStickyNoteClick,
      renderFoldPosition,
      translate,
    ]
  );

  return (
    <CellList
      {...rest}
      headerComponent={
        <Typography className="title accent" style={{ marginBottom: 24 }}>
          {title}
        </Typography>
      }
      gap={30}
      firstCell={
        <OutlinedButton
          className="sticky-notes-add "
          placeholder={translate("entities.stickyNotes.add")}
          onClick={onAdd}
        />
      }
      data={data}
      renderCell={renderCell}
      renderCellLoader={() => (
        <Loader
          className="sticky-note-loader"
          height={196}
          width={196}
          style={{ height: 196, width: 196 }}
        />
      )}
      listStyle={{ width: "fit-content" }}
      loaderListSize={12}
    />
  );
};

StickyNotesList.propTypes = stickyNotesPropTypes;

StickyNotesList.defaultProps = stickyNotesDefaultProps;

export { StickyNotesList };
