import api from "~/shared/services/api";

export const getLinkedSponsors = () => api.get("/admin/meeting_config/partner");

export const getSponsorMeetingConfig = ({ partnerId }) =>
  api.get(`/admin/meeting_config`, { params: { partner_id: partnerId } });

export const createSponsorMeetingConfig = ({ data }) =>
  api.post("admin/meeting_config", data);

export const updateSponsorMeetingConfig = ({ data, meetingConfigId }) =>
  api.put(`admin/meeting_config/${meetingConfigId}`, data);
