import React from "react";
import { Icon } from "~/shared/components/atoms";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { adminPaths } from "~/shared/routes/paths";
import { USER_TYPES } from "~/shared/utils/user";
import { useSigned } from "~/modules/signed/hooks";
import { CMSSideMenu } from "../../../../components/CMSSideMenu";

const ADMIN_SIDEBAR = {
  DASHBOARD: 1,
  SETTINGS: 2,
  USERS: 3,
  BROADCAST_ROOMS: 4,
  MARKET_ROOMS: 5,
  NETWORK_ROOMS: 6,
  CERTIFICATES: 7,
  MY_EVENTS: 8,
  POSTERS: 9,
  NOTIFICATIONS: 10,
  DOWNLOAD_MATERIALS: 11,
  EMAILS: 12,
  GAMIFICATION: 13,
};

/** This component has the responsabilty to deal with all
 * population for SideMenu of admin.
 */
const AdminSideMenu = () => {
  const { translate } = useTranslation();
  const { user } = useSigned();

  //* STATES
  const [data, setData] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    const generalModule = {
      id: 1,
      title: translate("common.general"),
      data: [
        {
          id: ADMIN_SIDEBAR.DASHBOARD,
          content: translate("common.dashboard"),
          icon: <Icon iconName="dashboard" />,
          path: adminPaths.dashboard.index,
        },
        {
          id: ADMIN_SIDEBAR.SETTINGS,
          content: translate("common.settings"),
          icon: <Icon iconName="settings" />,
          path: adminPaths.settings.index,
        },

        {
          id: ADMIN_SIDEBAR.EMAILS,

          content: translate("entities.emails.plural"),
          icon: <Icon iconName="email" />,
          path: adminPaths.emails.index,
        },
      ],
    };

    const contentModule = {
      id: 2,
      title: translate("common.content"),
      data: [
        {
          id: ADMIN_SIDEBAR.BROADCAST_ROOMS,
          content: translate("entities.broadcastRooms.plural"),
          icon: <Icon iconName="broadcastRooms" />,
          path: adminPaths.broadcastRooms.index,
        },
      ],
    };

    const audienceModule = {
      id: 3,
      title: translate("common.audience"),
      data: [
        {
          id: ADMIN_SIDEBAR.USERS,
          content: translate("entities.users.plural"),
          icon: <Icon iconName="users" />,
          path: adminPaths.users.index,
        },
      ],
    };

    //* Add MARKET ROOMS if has FLAG
    if (process.env.REACT_APP_HAS_MARKET_ROOMS === "true")
      contentModule.data.push({
        id: ADMIN_SIDEBAR.MARKET_ROOMS,
        content: translate("entities.marketRooms.singular"),
        icon: <Icon iconName="marketRooms" />,
        path: adminPaths.marketRooms.index,
      });

    //* Add GAMIFICATION if has FLAG
    if (process.env.REACT_APP_HAS_GAMIFICATION === "true")
      generalModule.data.push({
        id: ADMIN_SIDEBAR.GAMIFICATION,
        content: translate("entities.gamification.singular"),
        icon: <Icon iconName="gamification" />,
        path: adminPaths.gamification.index,
      });

    //* Add NETWORK ROOMS if has FLAG
    if (process.env.REACT_APP_HAS_NETWORK_ROOMS === "true")
      contentModule.data.push({
        id: ADMIN_SIDEBAR.NETWORK_ROOMS,
        content: translate("entities.networkRooms.plural"),
        icon: <Icon iconName="networkRooms" />,
        path: adminPaths.networkRooms.index,
      });

    //* Add CERTIFICATES if has FLAG
    if (process.env.REACT_APP_HAS_CERTIFICATE === "true")
      audienceModule.data.push({
        id: ADMIN_SIDEBAR.CERTIFICATES,
        content: translate("entities.certificates.plural"),
        icon: <Icon iconName="certificates" />,
        path: adminPaths.certificates.index,
      });

    //* Add MY EVENT if has FLAG
    if (process.env.REACT_APP_HAS_MY_SYMPLA_EVENTS === "true")
      generalModule.data.push({
        id: ADMIN_SIDEBAR.MY_EVENTS,
        content: translate("entities.events.mySymplaEvents"),
        icon: <Icon iconName="marketRooms" />,
        path: adminPaths.events.index,
      });

    //* Add POSTERS if has FLAG
    if (process.env.REACT_APP_HAS_POSTERS === "true")
      contentModule.data.push({
        id: ADMIN_SIDEBAR.POSTERS,
        content: translate("entities.posters.plural"),
        icon: <Icon iconName="posters" />,
        path: adminPaths.posters.index,
      });

    //* ADD Notifications
    contentModule.data.push({
      id: ADMIN_SIDEBAR.NOTIFICATIONS,
      content: translate("entities.notifications.plural"),
      icon: <Icon iconName="notifications" />,
      path: adminPaths.notifications.index,
    });

    if (process.env.REACT_APP_HAS_DOWNLOAD_MATERIALS === "true") {
      contentModule.data.push({
        id: ADMIN_SIDEBAR.DOWNLOAD_MATERIALS,
        content: translate("entities.downloadMaterials.downloadableMaterials"),
        icon: <Icon iconName="download" />,
        path: adminPaths.downloadMaterials.index,
      });
    }

    let sideMenuModules = [generalModule, audienceModule, contentModule];

    if (user.access_type === USER_TYPES.EDITOR.value) {
      sideMenuModules = [contentModule];
    }

    setData(sideMenuModules);
  }, [translate, user.access_type]);

  return <CMSSideMenu data={data} />;
};

export { AdminSideMenu };
