import React from "react";
import PropTypes from "prop-types";
import { MdSettings, BsPip } from "react-icons/all";

//* CUSTOM IMPORTS
import { usePip } from "~/modules/signed/hooks/pip";
import { isListValid, isDataValid } from "~/shared/utils/validations";
import { setIcon } from "~/shared/utils/icons";
import {
  Container,
  IFrame,
  PlyrPlayer,
  ExtraPlayers,
  ExtraPlayerButton,
  Reactions,
  VideoControllers,
  VideoController,
  IconButton,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function BroadcastRoomPlayer({
  className,
  room,
  src,
  extraPlayers,
  type,
  controllers,
  broadcastRoomId,
}) {
  //* STATE
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isPlyrVisible, setIsPlyVisible] = React.useState(true);
  const [plyrController, setPlyrController] = React.useState(null);

  const [videoSrc, setVideoSrc] = React.useState(src || null);
  const [videoType, setVideoType] = React.useState(type || null);
  const [videoAutoPlay, setVideoAutoPlay] = React.useState(false);
  const [videoCurrentTime, setVideoCurrentTime] = React.useState(0);

  const [extraPlayersState, setExtraPlayers] = React.useState([]);
  const [tabPosition, setTabPosition] = React.useState(0);
  const hasExtraPlayers = isListValid(extraPlayers);

  //* FUNCTION
  const { translate } = useTranslation();
  const { openPip, getPipVideoData, clearPipVideoData } = usePip();

  //* * If has extra player add a default button to return to initial player */
  React.useEffect(() => {
    if (hasExtraPlayers) {
      setExtraPlayers([
        {
          videoType: type,
          url: src,
          label: translate("entities.broadcastRooms.originalTransmission"),
          type: "camera",
        },
        ...extraPlayers,
      ]);
    }
  }, [extraPlayers, hasExtraPlayers, src, translate, type]);

  //* *Check if has pipVideo data to be override the video player controls */
  React.useEffect(() => {
    const pipVideoData = getPipVideoData();

    if (isDataValid(pipVideoData)) {
      const {
        src: pipSrc,
        type: pipType,
        currentTime,
        videoTabIndex,
        autoPlay,
      } = pipVideoData;

      setVideoType(pipType);
      setVideoSrc(pipSrc);
      setTabPosition(videoTabIndex);
      setVideoCurrentTime(currentTime);
      setVideoAutoPlay(autoPlay);
    }

    setIsLoaded(true);

    return () => clearPipVideoData();
  }, [clearPipVideoData, getPipVideoData]);

  function setPlayerType() {
    if (videoType === "youtube")
      return (
        isPlyrVisible && (
          <PlyrPlayer
            setController={setPlyrController}
            id="iframe-fragment"
            url={videoSrc}
            type={videoType}
            className="plyr-player"
            autoPlay={videoAutoPlay}
            currentTime={Number(videoCurrentTime)}
          />
        )
      );

    return (
      <IFrame
        className="iframe-player"
        id="iframe-fragment"
        allowfullscreen
        webkitallowfullscreen
        mozallowfullscreen
        frameborder="0"
        allow="
          accelerometer;
          autoplay;
          encrypted-media;
          fullscreen;
          microphone;
          camera;
          gyroscope;
          picture-in-picture;
          "
        src={videoSrc}
      />
    );
  }

  const onExtraPlayerButtonClick = (index, extraVideoUrl, extraVideoType) => {
    //* Clear PipVideo Data
    setVideoCurrentTime(0);
    setVideoAutoPlay(false);

    setTabPosition(index);

    //* Plyr player has to be unmounted to change his content, so if the player
    //* is already a youtube type, just unmounting, then mounting Plyr player again
    if (videoType === "youtube" && videoType === extraVideoType) {
      setIsPlyVisible(false);

      setTimeout(() => setIsPlyVisible(true), 1);
    }

    setVideoSrc(extraVideoUrl);

    setVideoType(extraVideoType);
  };

  const onPipButtonClick = () => {
    if (isDataValid(plyrController)) {
      openPip({
        src: videoSrc,
        title: room.title,
        subTitle: room.description,
        type: videoType,
        currentTime: plyrController.currentTime,
        videoTabIndex: tabPosition,
      });
    }
  };

  return (
    <Container className={className}>
      {hasExtraPlayers && (
        <ExtraPlayers>
          {extraPlayersState.map((extraPlayer, index) => (
            <ExtraPlayerButton
              key={extraPlayer.label}
              active={tabPosition === index}
              onClick={() =>
                onExtraPlayerButtonClick(
                  index,
                  extraPlayer.url,
                  extraPlayer.videoType
                )
              }
              type="button"
              title={extraPlayer.label}
            >
              {setIcon(extraPlayer.type)}
              <h4>{extraPlayer.label}</h4>
            </ExtraPlayerButton>
          ))}
        </ExtraPlayers>
      )}

      {isLoaded && setPlayerType()}

      <Reactions broadcastRoomId={broadcastRoomId} />

      <VideoControllers id="video-controllers">
        <MdSettings id="video-setting-button" title="Abrir menu de ações" />

        {controllers.map((controller, index) => (
          <VideoController key={controller.id}>
            <IconButton
              className="video-controller-button"
              title={controller.name}
              onClick={controller.onClick}
            >
              {controller.icon}
            </IconButton>

            {index === controllers.length - 1 && type === "youtube" && (
              <IconButton
                className="video-controller-button"
                title="Miniplayer"
                onClick={onPipButtonClick}
              >
                <BsPip />
              </IconButton>
            )}
          </VideoController>
        ))}
      </VideoControllers>
    </Container>
  );
}

BroadcastRoomPlayer.propTypes = {
  src: PropTypes.string.isRequired,
  className: PropTypes.string,
  type: PropTypes.string,
  extraPlayers: PropTypes.arrayOf(PropTypes.object),
  controllers: PropTypes.arrayOf(PropTypes.object),
  room: PropTypes.object.isRequired,
  broadcastRoomId: PropTypes.number,
};

BroadcastRoomPlayer.defaultProps = {
  type: "vimeo",
  className: "iframe-fragment",
  extraPlayers: [],
  controllers: [],
  broadcastRoomId: null,
};

export default BroadcastRoomPlayer;
