import React, { useState, useEffect } from "react";
import Ws from "@adonisjs/websocket-client";
import QRCode from "react-qr-code";
import { Link } from "react-router-dom";

import { useDispatch } from "react-redux";

//* ACTIONS
import { signInQrcode } from "~/modules/auth/store/auth/actions";

//* CUSTOM IMPORTS
import { Spinner } from "~/shared/components/atoms";
import { Container, Instructions, Instruction, QrcodeLoader } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function SignInQrcode() {
  //* STATES
  // eslint-disable-next-line no-unused-vars
  const [con, setCon] = useState(false);
  const [conId, setConId] = useState(null);
  const dispatch = useDispatch();

  //* FUNCTIONS
  const { translate } = useTranslation();

  useEffect(() => {
    const ws = Ws(process.env.REACT_APP_SOCKET_URL);

    ws.connect();
    const chat = ws.subscribe("session:login");

    chat.on("token_id", data => {
      setConId(data.id);
    });

    chat.on("auth", data => {
      dispatch(signInQrcode(data.authentication_token, data));
    });

    ws.on("open", () => {
      setCon(true);
    });

    ws.on("close", () => {
      setCon(false);
    });
  }, [dispatch]);

  return (
    <Container>
      <h3 className="auth-title"> {translate("pages.signInQrcode.title")}</h3>

      {conId !== null ? (
        <QRCode value={conId} size={238} />
      ) : (
        <QrcodeLoader>
          <Spinner />
        </QrcodeLoader>
      )}

      <Instructions>
        <Instruction>
          <strong>1</strong>
          <p>
            {translate("pages.signInQrcode.instruction1.part1")}{" "}
            {process.env.REACT_APP_NAME}{" "}
            {translate("pages.signInQrcode.instruction1.part2")}
            <a
              href={`${process.env.REACT_APP_CMS_APP_LINK}/users/store/index`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {" "}
              {translate("pages.signInQrcode.instruction1.part3")}
            </a>{" "}
            {translate("pages.signInQrcode.instruction1.part4")}
          </p>
        </Instruction>

        <Instruction>
          <strong>2</strong>
          <p>
            {translate("pages.signInQrcode.instruction2.part1")}{" "}
            <strong>
              {translate("pages.signInQrcode.instruction2.part2")}
            </strong>
          </p>
        </Instruction>

        <Instruction>
          <strong>3</strong>
          <p>
            {translate("pages.signInQrcode.instruction3.part1")}{" "}
            <strong>
              {" "}
              {translate("pages.signInQrcode.instruction3.part2")}
            </strong>
          </p>
        </Instruction>
      </Instructions>

      <footer className="auth-footer" style={{ height: 40 }}>
        <Link to="/login">{translate("pages.signInQrcode.link")}</Link>
      </footer>
    </Container>
  );
}

export default SignInQrcode;
