import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  getNotificationChartCount,
  getNotificationsUsersCountAccess,
} from "~/modules/signed/modules/cms/modules/admin/apis/notifications";
import { getNotificationsResume } from "~/modules/signed/modules/cms/modules/admin/apis/exports/notifications";

//* CUSTOM IMPORTS
import {
  getFullDateBR,
  getCurrentDay,
  getFullHours,
  isToday,
} from "~/shared/utils/dates";
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminNotifications } from "~/shared/routes/navigation";
import { AccessDetailsTemplate } from "~/shared/components/templates";
import { Icon, Typography } from "~/shared/components/atoms";
import { BarChart } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";

const notificationDetailsPropsTypes = {
  notificationId: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  action: PropTypes.oneOf([
    NOTIFICATION_TYPES.OPENED,
    NOTIFICATION_TYPES.RECEIVED,
  ]).isRequired,
  emptyMessage: PropTypes.string.isRequired,
  emptyDescription: PropTypes.string.isRequired,
  countDescription: PropTypes.string.isRequired,
  barChartTipDescription: PropTypes.string.isRequired,
};

const NotificationDetailsFragment = ({
  notificationId,
  title,
  action,
  emptyMessage,
  emptyDescription,
  countDescription,
  barChartTipDescription,
}) => {
  const { translate } = useTranslation();
  const { setBreadCrumbs } = useCMS();

  //* STATES
  const [dateInterval, setDateInterval] = React.useState({
    startDate: getCurrentDay({ inMidnigth: true, subtractDays: 0 }),
    endDate: new Date(),
  });
  const [cardData, setCardData] = React.useState({
    isLoaded: false,
    data: null,
  });
  const [tableData, setTableData] = React.useState({
    isLoaded: false,
    data: [],
  });
  const [search, setSearch] = React.useState("");
  const [pagination, setPagination] = React.useState({
    page: 1,
    lastPage: 1,
  });

  //* FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.notifications.plural"),
        function: openAdminNotifications,
      },
      {
        id: 2,
        name: title,
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, title, translate]);

  React.useEffect(() => {
    const { startDate, endDate } = dateInterval;

    const fetch = async () => {
      await getNotificationChartCount({
        notificationId,
        action,
        endDate,
        startDate,
        granularity:
          isToday({ date: startDate }) && isToday({ date: endDate })
            ? "hour"
            : "day",
      }).then(response => {
        setCardData({
          data: response.data,
          isLoaded: true,
        });
      });
    };

    fetch();
  }, [action, dateInterval, notificationId]);

  const fetchNotificationCountAccess = React.useCallback(
    page => {
      const { startDate, endDate } = dateInterval;

      const list = async () => {
        setTableData({
          data: [],
          isLoaded: false,
        });

        await getNotificationsUsersCountAccess({
          action,
          endDate,
          startDate,
          search,
          page,
          notificationId,
        }).then(response => {
          const { page: responsePage, lastPage, data } = response.data;

          setTableData({
            data,
            isLoaded: true,
          });

          setPagination({
            page: responsePage,
            lastPage,
          });
        });
      };

      list();
    },
    [action, dateInterval, notificationId, search]
  );

  React.useEffect(() => fetchNotificationCountAccess(1), [
    fetchNotificationCountAccess,
  ]);

  const renderTableRows = () => {
    const { data } = tableData;

    if (!data.length) return [];

    return data.map(row => {
      if (!row) return null;

      const { user, created_at: createdAt } = row;

      return {
        user: user && (
          <Typography className="subTitle bold accent">{user.name}</Typography>
        ),
        email: user && user.email,
        accessIn: `${getFullDateBR(createdAt)} - ${getFullHours(createdAt)}`,
      };
    });
  };

  const isBarLoaded = () => {
    if (cardData.data) if (cardData.data.total_count > 0) return true;

    return false;
  };

  return (
    <AccessDetailsTemplate
      onSearch={value => setSearch(value)}
      searchBarPlaceholder={translate("entities.users.search")}
      dateInterval={dateInterval}
      onDateSelect={date => setDateInterval(date)}
      downloadButtonData={{
        placeholder: translate("entities.reports.downloadReportInXlsx"),
        callDownloadSheetsApi: () =>
          getNotificationsResume({
            startDate: dateInterval.startDate,
            endDate: dateInterval.endDate,
            notificationId,
            action,
          }),
        sheetsName: title,
      }}
      cardData={{
        dotColor: "#FD9F2B",
        isLoaded: cardData.isLoaded,
        data: [
          {
            id: 1,
            counter: cardData.data && cardData.data.total_count,
            description: countDescription,
          },
        ],
        children: isBarLoaded() && (
          <BarChart
            granularity={cardData.data.granularity}
            chartData={cardData.data.data}
            tipDescription={barChartTipDescription}
          />
        ),
      }}
      tableData={{
        isLoading: !tableData.isLoaded,
        rows: tableData.isLoaded && renderTableRows(),
        titles: [
          translate("entities.users.singular"),
          translate("common.email"),
          translate("common.accessIn"),
        ],
        empty: {
          search,
          variant: search && "search",
          message: emptyMessage,
          description: emptyDescription,
          icon: <Icon iconName="notifications" />,
        },
      }}
      tablePagination={{
        currentPage: pagination.page,
        lastPage: pagination.lastPage,
        handlePagination: page => fetchNotificationCountAccess(page),
      }}
    />
  );
};

NotificationDetailsFragment.propTypes = notificationDetailsPropsTypes;

export { NotificationDetailsFragment };
