import React from "react";
import { PropTypes } from "prop-types";

import {
  Container,
  UserInfo,
  RankingInfo,
  Awards,
  Position,
  Name,
  Icon,
} from "./styles";
import { Avatar, avatarPropTypes } from "../../atoms";

const podiumPropTypes = {
  /** Base height for the podium */
  size: PropTypes.number,
  /** All props for <b>Avatar</b> component. */
  avatar: PropTypes.shape(avatarPropTypes),
  /** User name */
  name: PropTypes.string,
  /** Position of the user in the podium */
  position: PropTypes.string,
  /** Total points of the user */
  awards: PropTypes.string,

  /** icon name, if different from null renders an icon on top of the avatar */
  icon: PropTypes.string,
};

const podiumPropTypesDefaultProps = {
  size: 160,
  name: null,
  avatar: null,
  position: null,
  awards: null,
  icon: null,
};

function Podium({ position, name, awards, size, avatar, icon, ...rest }) {
  return (
    <Container {...rest}>
      <UserInfo>
        {icon && <Icon iconName={icon} size={32} />}

        {avatar && <Avatar {...avatar} style={{ margin: "0 auto" }} />}

        <Name className="centered subTitle bold" maxLines={2}>
          {name}
        </Name>
      </UserInfo>
      <RankingInfo size={size * 0.7}>
        <Position>{position}º</Position>
        <Awards className="breakWord" maxLines={1}>
          {awards}pts
        </Awards>
      </RankingInfo>
    </Container>
  );
}
Podium.propTypes = podiumPropTypes;
Podium.defaultProps = podiumPropTypesDefaultProps;

export { Podium, podiumPropTypes, podiumPropTypesDefaultProps };
