import React from "react";
import PropTypes from "prop-types";
import {
  Grid,
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";

const virtualizedTablePropTypes = {
  /** Function to render the cell at Table, return the current cell value,
   * rowIndex and column index */
  renderCell: PropTypes.func.isRequired,

  /** Data is an matriz */
  data: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.object)).isRequired,

  /** Define the default value for width in Table */
  defaultWidth: PropTypes.number.isRequired,
};

const virtualizedTableDefaultProps = {};

/** VirtualizedTable is commonly used for render a Table with many data */
const VirtualizedTable = ({ data, renderCell, defaultWidth }) => {
  const cache = new CellMeasurerCache({
    fixedWidth: true,
    defaultHeight: 40,
    defaultWidth,
  });

  const rowCount = data.length;
  const columnCount = data[0].length;

  const tableRef = React.useRef(null);

  const cellRenderer = ({ columnIndex, rowIndex, key, style, parent }) => {
    return (
      <CellMeasurer
        cache={cache}
        columnIndex={columnIndex}
        key={key}
        parent={parent}
        rowIndex={rowIndex}
      >
        {({ measure, registerChild }) => (
          <div ref={registerChild} style={style} key={key}>
            {renderCell(
              data[rowIndex][columnIndex],
              rowIndex,
              columnIndex,
              measure
            )}
          </div>
        )}
      </CellMeasurer>
    );
  };

  return (
    <AutoSizer>
      {({ width, height }) => (
        <Grid
          ref={tableRef}
          height={height}
          width={width}
          cellRenderer={cellRenderer}
          columnWidth={cache.defaultWidth}
          rowHeight={cache.rowHeight}
          deferredMeasurementCache={cache}
          rowCount={rowCount}
          columnCount={columnCount}
        />
      )}
    </AutoSizer>
  );
};

VirtualizedTable.propTypes = virtualizedTablePropTypes;

VirtualizedTable.defaultProps = virtualizedTableDefaultProps;

export {
  VirtualizedTable,
  virtualizedTablePropTypes,
  virtualizedTableDefaultProps,
};
