import React from "react";
import PropTypes from "prop-types";
import { Card, Typography } from "../../atoms";
import {
  VideoPlayer,
  videoPlayerPropTypes,
  videoPlayerDefaultProps,
} from "../../molecules/VideoPlayer";

//* CUSTOM IMPORTS
import { Film, Touchable } from "./styles";

const videoCardPropTypes = {
  /** Inherits all properties from VideoPlayer - check molecules/VideoPlayer for more information */
  videoPlayer: videoPlayerPropTypes,
  /** Render a title on the card */
  title: PropTypes.string,
  /** Render a sub title on the card */
  subTitle: PropTypes.string,
  /** Render a time string on the top of the card */
  time: PropTypes.string,
  onClick: PropTypes.func,
  withFilm: PropTypes.bool,
};

const videoCardDefaultProps = {
  videoPlayer: videoPlayerDefaultProps,
  title: null,
  subTitle: null,
  time: null,
  onClick: () => {},
  withFilm: false,
};

function VideoCard({
  videoPlayer,
  title,
  subTitle,
  time,
  onClick,
  withFilm,
  ...rest
}) {
  return (
    <Touchable onClick={onClick}>
      {withFilm && <Film />}

      <Card style={{ padding: "20px 10px 10px 10px" }} {...rest}>
        <Typography className="opaque">{time}</Typography>

        <Typography className="subTitle bold">{title}</Typography>

        <Typography className="opaque">{subTitle}</Typography>

        <VideoPlayer style={{ marginTop: 20 }} {...videoPlayer} />
      </Card>
    </Touchable>
  );
}

VideoCard.propTypes = videoCardPropTypes;
VideoCard.defaultProps = videoCardDefaultProps;

export { VideoCard, videoCardPropTypes, videoCardDefaultProps };
