import styled, { css } from "styled-components";

export const CustomButtonContent = styled.div`
  opacity: 0;
  position: absolute;
  margin-top: 12px;
  top: 0;
  right: 0;
`;

export const Container = styled.div`
  &:hover,
  &:active {
    .schedule-list-custom-button {
      opacity: 1;
      transition: opacity 0.5s;
    }
  }
  .schedule-cell-tag {
    display: none;
  }

  ${props =>
    props.isLive &&
    css`
      position: relative;

      .schedule-cell-tag {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
      }
      .schedule-list-bookmark {
        opacity: 1;
        position: absolute;
        bottom: -20px;
        right: -10px;
      }
    `}

    ${props =>
      props.bookmark &&
      css`
        position: relative;
      `}

  ${props => {
    switch (props.variant) {
      case "live":
        return css`
          display: block;

          .schedule-cell-times {
            display: none;
          }

          .schedule-cell-tag {
            display: block;
            margin-bottom: 16px;
          }
        `;

      default:
        return css`
          display: flex;
          align-items: center;
          .schedule-cell-right {
            margin-left: 20px;
          }

          .schedule-cell-description,
          .schedule-cell-stars {
            display: none;
          }
        `;
    }
  }}
`;
