import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, MaterialSlider } from "./styles";

const sliderPropTypes = {
  /** Callback that return the slider position */
  onChange: PropTypes.func,

  /** Initial slider position */
  initialValue: PropTypes.number,
};

const sliderDefaultProps = {
  onChange: () => {},
  initialValue: null,
};

/** Sliders allow users to select a value from a fixed set of options. */
function Slider({ initialValue, onChange, ...rest }) {
  const [value, setValue] = React.useState(
    initialValue >= 0 ? initialValue : 30
  );

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);

    onChange(newValue);
  };

  return (
    <Container {...rest}>
      <MaterialSlider
        value={typeof value === "number" ? value : 0}
        onChange={handleSliderChange}
        aria-labelledby="input-slider"
      />
    </Container>
  );
}

Slider.propTypes = sliderPropTypes;
Slider.defaultProps = sliderDefaultProps;

export { Slider, sliderPropTypes, sliderDefaultProps };
