import api from "~/shared/services/api";

//* CHATS
export const listUserChats = ({ page, search = "" }) =>
  Promise.resolve(
    api.get("/chat", {
      params: {
        page,
        search,
      },
    })
  );

export const createUserChat = ({ userIds, type }) =>
  Promise.resolve(
    api.post("/chat", {
      type,
      user_ids: userIds,
    })
  );

//* CHAT USERS
export const listUserChatUsers = ({ page, search = "" }) =>
  Promise.resolve(
    api.get("/chat_user", {
      params: {
        page,
        search,
      },
    })
  );

//* CHAT MESSAGES
export const getChatMessages = ({ page, chatId }) =>
  Promise.resolve(
    api.get("/chat_message", { params: { chat_id: chatId, page } })
  );

export const getUnreadedMessages = () =>
  Promise.resolve(api.get("/chat_message_log/unread_chat"));

//* UPDATE SETTINGS
export const updateSettings = ({ chatId, isSilenced }) =>
  api.put(`/chat_user/${chatId}`, { is_silenced: isSilenced });
