import styled, { keyframes } from "styled-components";

import { medias } from "~/shared/styles";
import { Typography } from "~/shared/components/atoms";

const showAnimation = keyframes`
  from{
    left: -300px;
  }

  to{
    flex: 1;
    left: 6.8%;
  }
`;

const closeAnimation = keyframes`
  from{
    flex: 1;
    left: 6.8%;
  }

  to{
    left: -300px;
  }
`;

export const GreenBackground = styled.div`
  height: 100vh;
  width: 100vw;
  position: relative;
  padding: 0 6.8%;
  display: flex;
`;

export const QuestionContainer = styled.div`
  position: absolute;
  bottom: 0;
  margin-bottom: 30px;
  min-width: 40.1%;
  max-width: 88%;
  animation: ${props => (props.isClosing ? closeAnimation : showAnimation)} 0.5s;

  @media (max-width: ${medias.mobile}) {
    max-width: calc(100% - 13.6%);
  }
`;

export const QuestionHeader = styled.div`
  background-color: ${props => props.theme.colors.primary};
  color: #fff;
  border-radius: 8px;
  padding: 6px 56px 6px 8px;
  height: 96px;
  display: flex;
  align-items: flex-start;
  top: -38px;
  left: -4px;
  position: absolute;

  img {
    height: 27.5px;
    border-right: 1px solid #ffffff54;
    margin-right: 10px;
    padding-right: 10px;
  }
`;

export const Title = styled(Typography)`
  margin-top: 1px;
`;

export const QuestionMessage = styled.div`
  display: flex;
  border-radius: 8px;
  background-color: ${props => props.theme.colors.surface};
  border-radius: 8px;
  padding: 30px;
  font-size: 22px;
  position: relative;
  z-index: 1;
  padding-right: 13.9%;
`;

export const Container = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
`;
