import types from "./types";

// LIST
export function listGroups() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listGroupsSuccessful(groups) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { groups },
  };
}
export function clearGroups() {
  return {
    type: types.LIST.CLEAR,
  };
}

// CREATE
export function createGroup(group) {
  return {
    type: types.CREATE.REQUEST,
    payload: { group },
  };
}
export function createGroupSuccessful(group) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { group },
  };
}
export function createGroupFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateGroup(group, groupId) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { group, groupId },
  };
}
export function updateGroupSuccessful(group) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { group },
  };
}
export function updateGroupFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteGroup(groupId) {
  return {
    type: types.DELETE.REQUEST,
    payload: { groupId },
  };
}
export function deleteGroupSuccessful(groupId) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { groupId },
  };
}
export function deleteGroupFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// FORM
export function clearGroupForm() {
  return {
    type: types.FORM.CLEAR,
  };
}
