import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

import { MdFileDownload, MdLock } from "react-icons/md";
import { Tag } from "../../../atoms";
import BannerMusic from "../../../../assets/static/bannerMusic2x.png";
import BannerVideo from "../../../../assets/static/bannerVideo2x.png";
import BannerDoc from "../../../../assets/static/bannerDoc2x.png";

import {
  Options,
  Card,
  Title,
  Storage,
  Banner,
  Content,
  Touchable,
  IconButton,
  PrivateOverlayIcon,
  PrivateOverlay,
} from "./styles";

const downloadMaterialCellPropTypes = {
  title: PropTypes.string.isRequired,

  fileType: PropTypes.string,

  fileSize: PropTypes.string,

  /** thumbnail is a link for thumbnail. */
  thumbnail: PropTypes.string,

  /** url is a link for download. */
  url: PropTypes.string,

  /** If <b>true</b> add an <b>Options</b> to the component and remove <b>DownloadButton</>. */
  isAdmin: PropTypes.bool,

  /** If <b>true</b> add an <b>Overlay</b> with an 'block' icon. */
  isBlocked: PropTypes.bool,

  /** Callback that is triggered when user click into <b>EditButton</b>. */
  onEdit: PropTypes.func,

  /** Callback that is triggered when user click into <b>DeleteButton</b>. */
  onDelete: PropTypes.func,

  /** Callback that is triggered when user click into <b>DownloadButton</b>. */
  onDownload: PropTypes.func,
};

const downloadMaterialCellDefaultProps = {
  isAdmin: false,
  isBlocked: false,
  url: null,
  fileType: null,
  fileSize: null,
  thumbnail: null,
  onEdit: () => {},
  onDelete: () => {},
  onDownload: () => {},
};

const DownloadMaterialCell = ({
  isAdmin,
  isBlocked,
  url,
  title,
  fileType,
  fileSize,
  thumbnail,
  onEdit,
  onDelete,
  onDownload,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);

  const handleChangeBanner = () => {
    if (!thumbnail) {
      if (fileType === "doc" || fileType === "pdf" || fileType === "txt")
        return BannerDoc;
      if (fileType === "mp4" || fileType === "avi" || fileType === "wmv")
        return BannerVideo;
      if (fileType === "mp3" || fileType === "mpeg") return BannerMusic;
    } else {
      return thumbnail;
    }

    return url;
  };

  return (
    <Touchable isBlocked={isBlocked}>
      <Card {...rest}>
        {!isAdmin && (
          <a
            href={url}
            target="_blank"
            onClick={onDownload}
            rel="noopener noreferrer"
          >
            <IconButton icon={<MdFileDownload size={20} />} />
          </a>
        )}
        {isAdmin && (
          <Options
            className="options"
            entity="Material"
            entityName={title}
            onEdit={onEdit}
            onDelete={onDelete}
          />
        )}
        <Banner
          src={handleChangeBanner}
          alt="preview"
          onClick={isAdmin && onEdit}
        >
          {isBlocked && <PrivateOverlay />}

          {isBlocked && (
            <PrivateOverlayIcon>
              <MdLock />
            </PrivateOverlayIcon>
          )}
        </Banner>

        <Content isBlocked={isBlocked} onClick={isAdmin && onEdit}>
          <Tag borderRadius={10} color={colors.accent}>
            {fileType}
          </Tag>
          <Title maxLines={3}>{title}</Title>
          <Storage>{fileSize}</Storage>
        </Content>
      </Card>
    </Touchable>
  );
};

DownloadMaterialCell.propTypes = downloadMaterialCellPropTypes;

DownloadMaterialCell.defaultProps = downloadMaterialCellDefaultProps;

export { DownloadMaterialCell, downloadMaterialCellPropTypes };
