import React from "react";

// CUSTOM IMPORTS
import UserForm from "~/modules/signed/modules/cms/modules/admin/pages/Users/pages/components/UserForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminUsers } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";

function CreateUser() {
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();
  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.users.plural"),
        function: openAdminUsers,
      },
      {
        id: 2,
        name: translate("entities.users.add"),
        function: () => {},
      },
    ]);

    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return <UserForm />;
}

export default CreateUser;
