/* eslint-disable no-unused-vars */
import React from "react";

//* CUSTOM IMPORTS
import {
  Form,
  formPropTypes,
  formDefaultProps,
} from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const adminFormPropTypes = {
  onSubmit: formPropTypes.onSubmit,

  yupValidation: formPropTypes.yupValidation,
};

const adminFormDefaultProps = {
  onSubmit: formDefaultProps.onSubmit,

  yupValidation: formDefaultProps.yupValidation,
};

/** This component extends <b>FormModal</b> to add some specific validations.
 * To see all props consult <b>Molecules/Form</b>
 */
const AdminForm = ({ onSubmit, yupValidation, ...rest }) => {
  //* REFs
  const formRef = React.useRef(null);

  //* FUNCTIONS
  const { translate } = useTranslation();
  const { defaultLanguage } = useDynamicTranslation();

  const handleSubmit = data => {
    let cancelSubmit = false;

    if (defaultLanguage)
      /** Convert translations */
      Object.entries(data).forEach(([key, value]) => {
        if (String(key).includes("-translation")) {
          const [translateKey, _] = key.split("-");
          const jsonTranslation = JSON.parse(value);

          let isFieldRequired = false;

          /** Check if the input is required */
          if (yupValidation[translateKey]) {
            const { _exclusive } = yupValidation[translateKey];

            if (_exclusive.required) isFieldRequired = true;
          }

          /** If input is required check if he has the default lang value */
          if (isFieldRequired)
            if (jsonTranslation[defaultLanguage.ref_code] === "") {
              formRef.current.setFieldError(
                translateKey,
                `${translate("components.translateForm.error.part1")} ${
                  defaultLanguage.title
                }`
              );
              cancelSubmit = true;
            }

          data[translateKey] = jsonTranslation;
          delete data[key];
        }
      });

    if (!cancelSubmit) onSubmit(data);
  };

  return (
    <Form
      {...rest}
      fowardRef={formRef}
      yupValidation={yupValidation}
      onSubmit={handleSubmit}
    />
  );
};

AdminForm.propTypes = adminFormPropTypes;

AdminForm.defaultProps = adminFormDefaultProps;

export { AdminForm, adminFormPropTypes, adminFormDefaultProps };
