import React from "react";
import PropTypes from "prop-types";

// ACTIONS
import { REF_TYPES } from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

// CUSTOM IMPORTS
import { ScrollContainer } from "./styles";
import { isDataValid } from "~/shared/utils/validations";
import { ExtraPlayerList } from "~/modules/signed/modules/cms/components/lists/ExtraPlayerList";

import { useTranslation } from "~/shared/hooks/translation";

function AccessibilityTab({ broadcastRoom }) {
  const { translate } = useTranslation();

  const refId = isDataValid(broadcastRoom) ? broadcastRoom.id : null;

  return (
    <ScrollContainer>
      <ExtraPlayerList
        title={translate("entities.tabs.broadcastTabsExtrasBroadcast")}
        buttonLabel={translate(
          "entities.broadcastRooms.accessibilityTabButtonLabel"
        )}
        refId={refId}
        refType={REF_TYPES.BROADCAST_EXTRA_PLAYER}
      />
    </ScrollContainer>
  );
}

AccessibilityTab.propTypes = {
  broadcastRoom: PropTypes.object.isRequired,
};

export default AccessibilityTab;
