import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORT
import { Icon, Button } from "../../../atoms";
import { SimpleCell } from "./styles";

const extraButtonCellPropTypes = {
  /** String that will be rendered inside the button */
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string])
    .isRequired,

  /** Render an icon at rigth of title, you can see all <b>iconNames</b> into
   * <b>shared/utils/IconUtils</b>
   */
  iconName: PropTypes.string,
};

const extraButtonCellDefaultProps = {
  iconName: null,
};

/** This components extends <b>SimpleCell</b>, see all props in
 * <b>Organisms/SimpleCell</b>
 */
const ExtraButtonCell = ({ children, iconName, ...rest }) => (
  <SimpleCell {...rest}>
    <Button icon={<Icon iconName={iconName} variant="white" />}>
      {children}
    </Button>
  </SimpleCell>
);

ExtraButtonCell.propTypes = extraButtonCellPropTypes;

ExtraButtonCell.defaultProps = extraButtonCellDefaultProps;

export {
  ExtraButtonCell,
  extraButtonCellPropTypes,
  extraButtonCellDefaultProps,
};
