import React from "react";
import PropTypes from "prop-types";
import { FaWhatsapp } from "react-icons/fa";

// CUSTOM IMPORTS
import { isStringEmpty } from "../../../utils/strings";
import { Container, Button, Message } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const whatsAppButtonPropTypes = {
  /** String link */
  link: PropTypes.string.isRequired,
};

/** Link to support on whatsapp */
function WhatsAppButton({ link }) {
  // FUNCTIONS
  const { translate } = useTranslation();

  function openWhatsapp() {
    window.open(link);
  }

  return (
    !isStringEmpty(link) && (
      <Container>
        <Button id="whatsapp-button" type="button">
          <FaWhatsapp />
        </Button>

        <Message id="message" type="button" onClick={openWhatsapp}>
          {translate("components.whatsappButton.message")}
        </Message>
      </Container>
    )
  );
}

WhatsAppButton.propTypes = whatsAppButtonPropTypes;

export { WhatsAppButton, whatsAppButtonPropTypes };
