import React from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

// ACTIONS
import { setGroupIds } from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";

// CUSTOM IMPORTS
import GroupList from "~/modules/signed/modules/cms/components/lists/GroupList";
import { ROOM_VIEW_TYPE } from "~/shared/utils/types";
import {
  Scroll,
  Container,
  Form,
  Fieldset,
  FieldsetTitle,
  Dropdown,
} from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function BroadcastVisibility({ viewType, setViewType }) {
  const { translate } = useTranslation();

  // REDUCER
  const dispatch = useDispatch();
  const { groupIds } = useSelector(state => state.adminBroadcastRooms.object);

  function convertViewType() {
    switch (viewType) {
      case ROOM_VIEW_TYPE.PUBLIC:
        return translate("common.public");

      case ROOM_VIEW_TYPE.PRIVATE:
        return translate("common.private");

      case ROOM_VIEW_TYPE.HIDE:
        return translate("common.hide");

      default:
        return null;
    }
  }

  function handleOnChange(value) {
    setViewType(value);
  }

  const viewTypes = [
    { id: 1, name: translate("common.public"), value: ROOM_VIEW_TYPE.PUBLIC },
    { id: 2, name: translate("common.private"), value: ROOM_VIEW_TYPE.PRIVATE },
    { id: 3, name: translate("common.hide"), value: ROOM_VIEW_TYPE.HIDE },
  ];

  return (
    <Scroll>
      <Container>
        <Form>
          <Fieldset>
            <FieldsetTitle>
              {translate("entities.broadcastRooms.roomVisibility")}
            </FieldsetTitle>
            <Dropdown
              name="view_type"
              options={viewTypes}
              noRegistration
              initialValue={convertViewType()}
              onChange={value => handleOnChange(value)}
            />
          </Fieldset>
        </Form>

        <GroupList
          title={translate("common.groups")}
          style={{ minWidth: 350, marginTop: 16 }}
          selectedGroups={groupIds}
          setSelectedGroups={partners => dispatch(setGroupIds(partners))}
        />
      </Container>
    </Scroll>
  );
}

BroadcastVisibility.propTypes = {
  viewType: PropTypes.string.isRequired,
  setViewType: PropTypes.func.isRequired,
};

export default BroadcastVisibility;
