import React from "react";

//* CUSTOM IMPORTS
import { Icon, Typography } from "~/shared/components/atoms";
import { Empty } from "~/shared/components/molecules";
import { TagSchedule } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { useSponsorSchedule } from "../../hooks";

const SponsorSchedulePreview = () => {
  const { availableTimeCourses, handleBlockedHour } = useSponsorSchedule();
  const { translate } = useTranslation();

  const hasTimeCourses = availableTimeCourses.length > 0;

  return (
    <>
      <TagSchedule
        withScroll
        scrollStyle={{ maxHeight: 370 }}
        data={availableTimeCourses}
        title={hasTimeCourses && translate("common.chooseATime")}
        onClick={tag => {
          const { hour } = tag;

          handleBlockedHour(hour.startDate, hour.endDate);
        }}
        emptyComponent={
          <Empty
            icon={<Icon iconName="schedule" />}
            message={translate("entities.meetings.form.emptyPreview.message")}
            description={translate(
              "entities.meetings.form.emptyPreview.description"
            )}
            style={{ height: "100%", width: 450 }}
          />
        }
      />
      {hasTimeCourses && (
        <Typography style={{ marginTop: 16 }}>
          {translate("entities.meetings.preview.footerMessage")}
        </Typography>
      )}
    </>
  );
};

export { SponsorSchedulePreview };
