import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import { deletePartner } from "~/modules/signed/modules/cms/modules/admin/store/partners/actions";

// CUSTOM IMPORTS
import { OutlinedButton } from "~/shared/components/atoms";
import { CATEGORY_LEVEL_TYPES } from "~/shared/utils/types";
import { useTranslation } from "~/shared/hooks/translation";
import DiamondCellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import CardLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { PartnerCell } from "~/shared/components/organisms/cells/PartnerCell";
import { CellList } from "~/shared/components/molecules";
import {
  openCreateMarketRoom,
  openEditMarketRoom,
} from "~/shared/routes/navigation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function Partners({ partners, isLoaded, marketRoomName, level, marketRoomId }) {
  const dispatch = useDispatch();
  const { dTranslate, dTranslateTags } = useDynamicTranslation();
  const { translate } = useTranslation();

  // STATE
  const { isDeleting } = useSelector(state => state.adminPartners.list);
  const [partnerId, setPartnerId] = React.useState(null);

  // FUNCTION
  const onDeletePartner = partner => {
    setPartnerId(partner.id);
    dispatch(deletePartner(partner.id, marketRoomId));
  };

  const handleOnCellEdit = partner =>
    openEditMarketRoom(marketRoomName, marketRoomId, partner.id);

  const renderCell = partner => {
    const {
      id,
      title,
      sub_title: subTitle,
      logo,
      tags,
      bio,
      list_wallpaper: wallpaper,
    } = partner && partner;

    return (
      <PartnerCell
        key={id}
        variant={level}
        banner={wallpaper}
        title={dTranslate(title)}
        subTitle={dTranslate(subTitle)}
        bio={dTranslate(bio)}
        avatar={logo}
        tags={dTranslateTags(tags)}
        onClick={() =>
          openEditMarketRoom(marketRoomName, marketRoomId, partner.id)
        }
        crudOptions={{
          entity: translate("entities.marketRooms.singular"),

          onEdit: () => handleOnCellEdit(partner),

          onDelete: () => onDeletePartner(partner),
        }}
      />
    );
  };

  const renderCellLoader = () => {
    if (level === CATEGORY_LEVEL_TYPES.DIAMOND.value)
      return <DiamondCellLoader />;

    if (level === CATEGORY_LEVEL_TYPES.GOLD.value)
      return <CardLoader width={300} height={169} />;

    if (level === CATEGORY_LEVEL_TYPES.SILVER.value)
      return <CardLoader width={130} height={130} />;

    return null;
  };

  return (
    <CellList
      cellId={partnerId}
      isDeleting={isDeleting}
      isLoaded={isLoaded}
      data={partners}
      gap={20}
      firstCell={
        <OutlinedButton
          style={{ width: 300, minHeight: 140 }}
          type="button"
          onClick={() => openCreateMarketRoom(marketRoomName, marketRoomId)}
          placeholder={translate("entities.marketRooms.addBooth")}
        />
      }
      containerStyle={{ marginBottom: 40 }}
      renderCellLoader={renderCellLoader}
      renderCell={partner => renderCell(partner)}
    />
  );
}

Partners.propTypes = {
  partners: PropTypes.arrayOf(PropTypes.object).isRequired,
  marketRoomName: PropTypes.string.isRequired,
  marketRoomId: PropTypes.number.isRequired,
  level: PropTypes.string.isRequired,
  isLoaded: PropTypes.bool.isRequired,
};

export default Partners;
