import React from "react";
import PropTypes from "prop-types";
import { MdFileUpload, MdDescription } from "react-icons/all";
import { useDropzone } from "react-dropzone";

//* CUSTOM IMPORTS
import { IconedButton, iconedButtonPropTypes, Typography } from "../../atoms";
import { Container, DropzoneContainer, RejectedFiles } from "./styles";

const dropzonePropTypes = {
  /** Define the placeholder for <b>Dropzone</b> */
  placeholder: iconedButtonPropTypes.placeholder.isRequired,

  /** Define the <b>Dropzone</b> icon, default is  an <b><MdFileUpload/></b> */
  icon: iconedButtonPropTypes.icon,

  /** Define the file formats to be accepted, default is "image/jpeg, image/png" */
  acceptedFormats: PropTypes.string,

  /** Define the max files number that can be uploaded, default is 1 file */
  maxFiles: PropTypes.number,

  /** Callback that is triggered when file is dropped,
   * return an array with all acceptedFiles
   */
  onDrop: PropTypes.func,

  /** Callback that is triggered when file is dropped, and the files
   * has been accepted, return an array with all acceptedFiles
   */
  onDropAccepted: PropTypes.func,

  /** Render an message in the center of the <b>Dropzone</b> */
  isLoading: PropTypes.bool,

  /** Define the message when the isLoading is TRUE */
  loadingMessage: PropTypes.string,

  style: PropTypes.object,

  /** Default variant is "default" */
  variant: PropTypes.oneOf(["default", "error", " filled"]),

  /** Render an error message below the <b>Dropzone</b> */
  errorMessage: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
};

const dropzoneDefaultProps = {
  icon: null,
  maxFiles: 1,
  acceptedFormats: null,
  onDropAccepted: () => {},
  onDrop: () => {},
  style: null,
  isLoading: false,
  loadingMessage: null,
  variant: "default",
  errorMessage: null,
};

/** Dropzone component is commonly used for catch user files via an Drop action.
 * This component override react-dropzone lib, see more here: https://github.com/react-dropzone/react-dropzone
 */
const Dropzone = ({
  icon,
  placeholder,
  acceptedFormats,
  maxFiles,
  onDrop,
  onDropAccepted,
  style,
  isLoading,
  loadingMessage,
  variant,
  errorMessage,
  ...rest
}) => {
  const handleOnDrop = React.useCallback(files => onDrop(files), [onDrop]);

  const handleOnDropAccepted = React.useCallback(
    files => onDropAccepted(files),
    [onDropAccepted]
  );

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    fileRejections,
    acceptedFiles,
  } = useDropzone({
    onDropAccepted: handleOnDropAccepted,
    onDrop: handleOnDrop,
    accept: acceptedFormats,
    maxFiles,
  });

  const renderVariant = () => {
    if (variant) return variant;

    if (isDragActive || acceptedFiles.length) return "filled";

    if (fileRejections.length) return "error";

    return null;
  };

  const fileAcceptedItems = acceptedFiles.map(file => (
    <IconedButton placeholder={file.name} icon={<MdDescription />} />
  ));

  const fileRejectionItems = fileRejections.map(({ file }) => (
    <li key={file.path}>
      O arquivo <strong>{file.name}</strong> não é compatível
    </li>
  ));

  const renderContent = () => {
    if (isLoading)
      return (
        <Typography className="subTitle accent bold">
          {loadingMessage || "Carregando..."}
        </Typography>
      );

    if (acceptedFiles.length) return fileAcceptedItems;

    return (
      <IconedButton
        className="dropzone-iconed-button"
        placeholder={placeholder}
        icon={icon || <MdFileUpload />}
      />
    );
  };

  return (
    <Container>
      <DropzoneContainer
        {...rest}
        variant={renderVariant()}
        {...getRootProps()}
        style={style}
      >
        <input {...getInputProps()} />

        {renderContent()}
      </DropzoneContainer>

      {errorMessage && (
        <Typography style={{ marginTop: 40 }} className="error bold">
          {errorMessage}
        </Typography>
      )}

      <RejectedFiles>{fileRejectionItems}</RejectedFiles>
    </Container>
  );
};

Dropzone.propTypes = dropzonePropTypes;

Dropzone.defaultProps = dropzoneDefaultProps;

export { Dropzone, dropzonePropTypes, dropzoneDefaultProps };
