import React from "react";
import PropTypes from "prop-types";
import { MdMoreVert, MdLock } from "react-icons/md";

//* CUSTOM IMPORTS
import { getFullDateBR, getFullHours } from "../../../../utils/dates";
import { isListValid } from "../../../../utils/validations";
import { Typography, Trans } from "../../../atoms";
import { TagGroup } from "../../../molecules";
import {
  Touchable,
  ContentWrapper,
  Content,
  Infos,
  Thumbnail,
  PrivateOverlay,
  PrivateOverlayIcon,
  LiveTag,
  CRUDOptionsMenu,
  CustomButtonContent,
} from "./styles";

const broadcastRoomCellPropTypes = {
  image: PropTypes.string,
  startDate: PropTypes.string,
  endDate: PropTypes.string,
  title: PropTypes.string.isRequired,
  speakers: PropTypes.arrayOf(PropTypes.object),
  tags: PropTypes.arrayOf(PropTypes.object),
  bottomTags: PropTypes.object,
  onClick: PropTypes.func,
  onLive: PropTypes.bool,
  isAdmin: PropTypes.bool,
  isBlocked: PropTypes.bool,
  crudOptions: PropTypes.object,
  bookmark: PropTypes.bool,
  id: PropTypes.number,
  customButton: PropTypes.element,
};

const broadcastRoomCellDefaultProps = {
  image: null,
  speakers: [],
  tags: [],
  bottomTags: null,
  bookmark: false,
  onClick: () => {},
  onLive: false,
  isAdmin: false,
  isBlocked: false,
  crudOptions: null,
  startDate: null,
  endDate: null,
  id: null,
  customButton: null,
};

function BroadcastRoomCell({
  image,
  tags,
  startDate,
  endDate,
  title,
  speakers,
  bottomTags,
  onClick,
  onLive,
  isAdmin,
  isBlocked,
  crudOptions,
  customButton,
  ...rest
}) {
  return (
    <Touchable isBlocked={isBlocked} {...rest}>
      <ContentWrapper isAdmin={isAdmin}>
        <Content onClick={onClick}>
          <Thumbnail src={image} className="thumbnail">
            {isBlocked && <PrivateOverlay />}

            {isBlocked && (
              <PrivateOverlayIcon>
                <MdLock />
              </PrivateOverlayIcon>
            )}
          </Thumbnail>

          <Infos isBlocked={isBlocked}>
            {onLive && <LiveTag variant="live">Ao vivo</LiveTag>}

            <TagGroup tags={tags} trim />

            {endDate && startDate && (
              <Typography className="opaque small" style={{ marginTop: 8 }}>
                {`${getFullDateBR(startDate)} - ${getFullHours(
                  startDate
                )} - ${getFullHours(endDate)}`}
              </Typography>
            )}

            <Typography className="subTitle bold" maxLines={2}>
              {title}
            </Typography>

            <Typography className="opaque small">
              {isListValid(speakers) && (
                <>
                  <Trans translationKey="common.with">Com</Trans>

                  <strong style={{ margin: "0 4px" }}>
                    {speakers[0].name}
                  </strong>
                  {speakers.length > 1 && (
                    <>
                      <Trans translationKey="common.and">e</Trans>

                      <strong style={{ margin: "0 4px" }}>
                        <Trans translationKey="common.more" />{" "}
                        {speakers.length - 1}
                      </strong>
                    </>
                  )}
                </>
              )}
            </Typography>
          </Infos>
        </Content>

        {bottomTags && (
          <TagGroup
            borderRadius={4}
            wrap
            {...bottomTags}
            style={{ marginTop: 8 }}
            tagsMaxWidth={100}
          />
        )}
      </ContentWrapper>

      {isAdmin && crudOptions && (
        <CRUDOptionsMenu
          className="live-room-cell-options"
          buttonIcon={<MdMoreVert />}
          {...crudOptions}
        />
      )}

      {customButton && (
        <CustomButtonContent className="room-custom-button">
          {customButton}
        </CustomButtonContent>
      )}
    </Touchable>
  );
}

BroadcastRoomCell.propTypes = broadcastRoomCellPropTypes;

BroadcastRoomCell.defaultProps = broadcastRoomCellDefaultProps;

export {
  BroadcastRoomCell,
  broadcastRoomCellPropTypes,
  broadcastRoomCellDefaultProps,
};
