import React from "react";

//* CUSTOM IMPORTS
import { Typography, Box } from "~/shared/components/atoms";
import { SwipeTabs, SearchBar } from "~/shared/components/molecules";
import { ExpandableContainer } from "~/shared/components/molecules/ExpandableContainer";
import { useTranslation } from "~/shared/hooks/translation";
import { MeetingList } from "./components/MeetingList";
import { MeetingDetails } from "./components/MeetingDetails";
import { SponsorMeetingsProvider, useSponsorMeetings } from "./hooks";
import { MY_SCHEDULE_TYPES } from "~/shared/utils/types";
import { useWindow } from "~/shared/hooks/window";
import { useSponsorCMS } from "../../hooks";

const Content = () => {
  const { translate } = useTranslation();
  const { isBelowWide } = useWindow();
  const { managedPartnerId } = useSponsorCMS();
  const { meetings } = useSponsorMeetings();

  //* STATES
  const [search, setSearch] = React.useState("");
  const [selectedSchedule, setSelectedSchedule] = React.useState(null);

  //* FUNCTIONS
  const closeDetails = () => {
    setSelectedSchedule(null);
  };

  React.useMemo(() => {
    if (managedPartnerId) {
      setSearch("");
      setSelectedSchedule(null);

      const searchBar = document.getElementById("sponsor-meeting-searchbar");

      if (searchBar) searchBar.value = "";
    }
  }, [managedPartnerId]);

  const handleOnScheduleItemClick = schedule => {
    setSelectedSchedule(null);

    if (selectedSchedule) {
      if (selectedSchedule.id === schedule.id) {
        closeDetails();
        return;
      }

      setSelectedSchedule(schedule);
    } else {
      setSelectedSchedule(schedule);
    }
  };

  const MY_SCHEDULE_TABS = [
    { id: 1, name: translate("entities.meetings.scheduledPlural") },
  ];

  const scheduleDetails = React.useMemo(() => {
    return (
      <Box
        style={{
          marginTop: isBelowWide ? 20 : 70,
          marginBottom: isBelowWide && 20,
        }}
      >
        {selectedSchedule &&
          selectedSchedule.type === MY_SCHEDULE_TYPES.MEETING && (
            <MeetingDetails data={selectedSchedule} onCancel={closeDetails} />
          )}
      </Box>
    );
  }, [isBelowWide, selectedSchedule]);

  return (
    <Box>
      <Typography className="centered bigTitle" style={{ marginBottom: 16 }}>
        {translate("entities.meetings.scheduledPlural")}
      </Typography>

      <SearchBar
        centerPlaceholder
        inputId="sponsor-meeting-searchbar"
        style={{ maxWidth: 450, margin: "0 auto" }}
        placeholder={translate("entities.meetings.searchUser")}
        onChange={value => {
          setSearch(value);
          closeDetails();
        }}
        isSearching={!meetings.isLoaded}
      />

      <ExpandableContainer
        style={{ marginTop: 40 }}
        isExpanded={!isBelowWide && selectedSchedule}
        leftChildren={
          <SwipeTabs
            size="small"
            alignment="left"
            tabs={MY_SCHEDULE_TABS}
            style={{ maxWidth: 458, margin: "0 auto" }}
          >
            <MeetingList
              selectedMeetingId={selectedSchedule && selectedSchedule.id}
              search={search}
              onItemClick={handleOnScheduleItemClick}
              detailsComponent={isBelowWide && scheduleDetails}
            />
          </SwipeTabs>
        }
        rightChildren={scheduleDetails}
      />
    </Box>
  );
};

const MySchedule = () => (
  <SponsorMeetingsProvider>
    <Content />
  </SponsorMeetingsProvider>
);

export default MySchedule;
