import React from "react";
import { MdAddCircle } from "react-icons/md";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import ScheduleForm from "./ScheduleForm";
import Schedule from "~/modules/signed/modules/cms/components/lists/BroadcastRoomScheduleList";
import {
  Container,
  LeftContainer,
  LeftScroll,
  RigthContainer,
  AddButton,
} from "./styles";
import { isDataValid } from "~/shared/utils/validations";

import { useTranslation } from "~/shared/hooks/translation";

function BroadcastRoomSchedule({ broadcastRoom }) {
  const { translate } = useTranslation();

  // STATES
  const [isCreating, setIsCreating] = React.useState(false);
  const [showSheduleForm, setShowScheduleForm] = React.useState(false);
  const [scheduleFormData, setScheduleFormData] = React.useState({});
  const [activedCell, setActivedCell] = React.useState(null);
  const [editMode, setEditMode] = React.useState(false);

  // FUNCTIONS
  function handleScheduleFormVisibility() {
    // Unmouting component
    setShowScheduleForm(false);
    setTimeout(() => setShowScheduleForm(true), 1);
  }

  function handleAddButtonClick() {
    setScheduleFormData({});
    setEditMode(false);
    setIsCreating(true);
    setActivedCell(null);
    setShowScheduleForm(true);
    handleScheduleFormVisibility();
  }

  function openEditForm(data) {
    setScheduleFormData(data);
    setActivedCell(data.id);
    setEditMode(true);

    setIsCreating(false);
    handleScheduleFormVisibility();
  }

  return (
    <Container>
      <LeftScroll id="left-scroll">
        <LeftContainer>
          <AddButton
            type="button"
            onClick={handleAddButtonClick}
            active={isCreating}
          >
            <MdAddCircle />
            {translate("entities.broadcastRooms.addSchedule")}
          </AddButton>

          <Schedule
            broadcastRoomId={
              (isDataValid(broadcastRoom) && broadcastRoom.id) || null
            }
            onCellClick={openEditForm}
            activedCell={activedCell}
          />
        </LeftContainer>
      </LeftScroll>

      {showSheduleForm && (
        <RigthContainer>
          <ScheduleForm
            editMode={editMode}
            broadcastRoomId={
              (isDataValid(broadcastRoom) && broadcastRoom.id) || null
            }
            onClose={() => setShowScheduleForm(false)}
            schedule={scheduleFormData}
          />
        </RigthContainer>
      )}
    </Container>
  );
}

BroadcastRoomSchedule.propTypes = {
  broadcastRoom: PropTypes.object,
};

BroadcastRoomSchedule.defaultProps = {
  broadcastRoom: null,
};

export default BroadcastRoomSchedule;
