import api from "~/shared/services/api";

export const getMarketRoomseportCount = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/marketroom/report/count", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );

export const getMarketRoomsReportCountAccess = ({
  startDate,
  endDate,
  page,
  perPage,
  search,
}) =>
  Promise.resolve(
    api.get("/admin/marketroom/report/count_access", {
      params: {
        start_date: startDate,
        end_date: endDate,
        page,
        per_page: perPage,
        search,
      },
    })
  );
