import api from "~/shared/services/api";

export const listSponsorMeetings = ({ partnerId, page, search }) =>
  api.get(`/admin/meeting/schedule`, {
    params: { page, search, partner_id: partnerId },
  });

export const deleteSponsorMeeting = ({ scheduledMeetingId }) =>
  api.delete(`/admin/meeting/schedule/${scheduledMeetingId}`);
