import React from "react";
import PropTypes from "prop-types";
import { useSelector, useDispatch } from "react-redux";
import { MdEdit } from "react-icons/md";

// ACTIONS
import {
  listTags,
  clearTags,
} from "~/modules/signed/modules/cms/modules/admin/store/tags/actions";

// CUSTOM IMPORTS
import { isListValid } from "~/shared/utils/validations";
import {
  Container,
  Title,
  Tags,
  Tag,
  AddTagButton,
  TagWrrapper,
  EditButton,
} from "./styles";
import TagLoaderList from "./LoaderList";
import {
  openCreateTagModal,
  openEditTagModal,
} from "~/shared/routes/navigation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

import { useTranslation } from "~/shared/hooks/translation";

function TagList({ className, selectedTags, setSelectedTags, type }) {
  // type props determine which type of tags has to be listed

  // REDUCER
  const dispatch = useDispatch();
  const tagListReducer = useSelector(state => state.adminTags.list);
  const { data: tags, isLoaded } = tagListReducer && tagListReducer;

  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) {
      dispatch(listTags(type));
    }

    return () => {
      dispatch(clearTags());
      isMounted = false;
    };
  }, [dispatch, type]);

  function handleSelectTag(tagId) {
    // Find the index of id
    const alreadySelected = selectedTags.findIndex(item => item === tagId);

    // If do not have de id inside array will return - 1
    if (alreadySelected >= 0) {
      // Receive all the array less the id
      const filteredTags = selectedTags.filter(item => item !== tagId);

      setSelectedTags(filteredTags);
    } else {
      setSelectedTags([...selectedTags, tagId]);
    }
  }

  return (
    <Container className={className}>
      <Title>TAGS</Title>

      {isLoaded ? (
        <Tags>
          {isListValid(tags) &&
            tags.map(tag => (
              <TagWrrapper>
                <Tag
                  selected={!!selectedTags.includes(tag.id)}
                  onClick={() => handleSelectTag(tag.id)}
                  type="button"
                  key={tag.id}
                >
                  {dTranslate(tag.name)}
                </Tag>

                <EditButton
                  className="tag-edit-button"
                  type="button"
                  title={`Editar ${dTranslate(tag.name)}`}
                  onClick={() => openEditTagModal(type, tag)}
                >
                  <MdEdit />
                </EditButton>
              </TagWrrapper>
            ))}

          <AddTagButton type="button" onClick={() => openCreateTagModal(type)}>
            {translate("entities.marketRooms.addTag")} +
          </AddTagButton>
        </Tags>
      ) : (
        <TagLoaderList />
      )}
    </Container>
  );
}

TagList.propTypes = {
  className: PropTypes.string,
  selectedTags: PropTypes.func.isRequired,
  setSelectedTags: PropTypes.func.isRequired,
  type: PropTypes.string.isRequired,
};

TagList.defaultProps = {
  className: null,
};

export default TagList;
