import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { SimpleCell, simpleCellDefaultProps } from "../../SimpleCell";
import { Typography, Box, Tag } from "../../../atoms";
import { Stars, starsPropTypes } from "../../../molecules";
import SpeakersAvatar from "../../lists/SpeakerAvatarViewList";
import SpeakerList from "../../lists/SpeakerViewList";
import { useTranslation } from "../../../../hooks/translation";
import { Container, CustomButtonContent } from "./styles";

const scheduleCellPropTypes = {
  variant: PropTypes.oneOf(["accent", "default", "live"]),

  /** The title of the schedule */
  title: PropTypes.string.isRequired,

  /** The description of the schedule */
  description: PropTypes.string,

  /** The startTime for schedule in Date string */
  startTime: PropTypes.string,

  /** The endTime for schedule in Date string */
  endTime: PropTypes.string,

  /** An Array of speaker picture to display  */
  speakers: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      picture: PropTypes.string,
    })
  ),
  /** If this props is <b>true</b>, disable the menu, default value is <b>false</b>  */
  disableMenu: PropTypes.bool,

  /** If <b>true</b> render an <b>LiveTag</b> at right of the component */
  isLive: PropTypes.bool,

  stars: PropTypes.shape(starsPropTypes),

  /** Render a button on the cell */
  customButton: PropTypes.element,

  /** Render a button on the cell, pass parameters by callback */
  renderCustomButton: PropTypes.func,

  liveTagContent: PropTypes.string,
};

const scheduleCellDefaultProps = {
  variant: "default",
  startTime: null,
  description: null,
  endTime: null,
  speakers: [],
  isLive: false,
  stars: null,
  disableMenu: simpleCellDefaultProps.disableMenu,
  customButton: null,
  renderCustomButton: () => {},
  liveTagContent: null,
};

const ScheduleCell = ({
  variant,
  title,
  description,
  startTime,
  endTime,
  speakers,
  disableMenu,
  isLive,
  liveTagContent,
  stars,
  customButton,
  renderCustomButton,
  ...rest
}) => {
  const { translate } = useTranslation();

  return (
    <SimpleCell
      variant={variant === "accent" && "accent"}
      entity={translate("common.schedule")}
      disableMenu={disableMenu}
      {...rest}
    >
      <Container variant={variant} isLive={isLive} className="schedule-cell">
        <Tag variant="live" className="schedule-cell-tag">
          {liveTagContent || translate("common.live")}
        </Tag>

        {startTime && endTime && (
          <Box className="schedule-cell-times">
            <Typography className="accent bold">{startTime}</Typography>
            <Typography className="bold">{endTime}</Typography>
          </Box>
        )}

        <Box className="schedule-cell-right">
          <Typography
            maxLines={2}
            className="bold subTitle"
            style={{ marginBottom: 4 }}
          >
            {title}
          </Typography>

          {stars && (
            <Stars
              {...stars}
              className="schedule-cell-stars"
              title={translate("common.rateThisSchedule")}
              style={{ margin: "8px 0 16px" }}
            />
          )}

          {description && (
            <Typography
              className="small schedule-cell-description"
              style={{ marginBottom: 16 }}
            >
              {description}
            </Typography>
          )}

          {variant !== "live" && speakers && (
            <SpeakersAvatar
              className="schedule-cell-speakers-avatar"
              speakers={speakers}
              maxSpeakers={3}
            />
          )}

          {variant === "live" && (
            <SpeakerList
              className="schedule-cell-speakers"
              speakers={speakers}
              renderCustomButton={renderCustomButton}
            />
          )}

          {customButton && (
            <CustomButtonContent className="schedule-list-custom-button">
              {customButton}
            </CustomButtonContent>
          )}
        </Box>
      </Container>
    </SimpleCell>
  );
};

ScheduleCell.propTypes = scheduleCellPropTypes;

ScheduleCell.defaultProps = scheduleCellDefaultProps;

export { ScheduleCell, scheduleCellPropTypes, scheduleCellDefaultProps };
