import React from "react";
import PropTypes from "prop-types";
import MaterialIconButton from "@material-ui/core/IconButton";

//* CUSTOM IMPORTS
import { Container } from "./styles";
import { Tip } from "../index";

const iconButtonPropTypes = {
  className: PropTypes.string,

  /** preferable to be an icon */
  children: PropTypes.element.isRequired,

  /** Define the color of the icon, default is smoked */
  color: PropTypes.string,

  size: PropTypes.number,

  variant: PropTypes.oneOf(["default", "filled"]),

  /** Define the button background */
  tipPosition: PropTypes.oneOf(["top", "botton", "right", "left"]),

  /** If tip has some string, render an Tip when user hover the button */
  tip: PropTypes.element,

  /** if TRUE change the icon color */
  isSelected: PropTypes.bool,

  /** Is width of sizeTip */
  sizeTip: PropTypes.string,
};

const iconButtonDefaultProps = {
  className: null,
  color: null,
  size: 45,
  tip: null,
  tipPosition: "top",
  isSelected: false,
  variant: "default",
  sizeTip: null,
};

/** IconButton is commonly used when you want a icon to be clickable */
function IconButton({
  children,
  className,
  color,
  size,
  tip,
  tipPosition,
  variant,
  sizeTip,
  ...rest
}) {
  return (
    <Container
      variant={variant}
      className={className}
      color={color}
      size={size}
    >
      <MaterialIconButton variant="contained" {...rest}>
        {children}
      </MaterialIconButton>

      {tip && (
        <Tip
          className="icon-button-tip"
          content={tip}
          direction={tipPosition}
          size={sizeTip}
          opacity={1}
        />
      )}
    </Container>
  );
}

IconButton.propTypes = iconButtonPropTypes;

IconButton.defaultProps = iconButtonDefaultProps;

export { IconButton, iconButtonPropTypes, iconButtonDefaultProps };
