import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Icons, Icon, Title } from "./styles";
import { setIcon, icons } from "~/shared/utils/icons";
import { useTranslation } from "~/shared/hooks/translation";

function ButtonIcons({ className, setSelectedIcon, selectedIcon }) {
  const { translate } = useTranslation();

  // FUNCTION

  function handleIconClick(iconType) {
    // If click in the same icon, disable icon clicked
    if (iconType === selectedIcon) {
      setSelectedIcon(null);
    } else {
      setSelectedIcon(iconType);
    }
  }

  return (
    <Container className={className}>
      <Title>{translate("common.icons")}</Title>
      <Icons>
        {icons.map(icon => (
          <Icon
            active={selectedIcon === icon.type}
            type="button"
            key={icon.id}
            onClick={() => handleIconClick(icon.type)}
          >
            {setIcon(icon.type)}
          </Icon>
        ))}
      </Icons>
    </Container>
  );
}

ButtonIcons.propTypes = {
  className: PropTypes.string,
  setSelectedIcon: PropTypes.func.isRequired,
  selectedIcon: PropTypes.string.isRequired,
};

ButtonIcons.defaultProps = {
  className: "",
};

export default ButtonIcons;
