import React, { useCallback } from "react";
import {
  NotificationCardPropTypes,
  NotificationCardDefaultProps,
} from "../propTypes";

//* CUSTOM IMPORTS
import { ProgressBar, Icon } from "../../../atoms";
import { FooterButtons } from "../..";
import {
  Card,
  Title,
  Description,
  NotificationIcon,
  UnreadLink,
  Close,
  ProgressBarContainer,
} from "./styles";
import { stopPropagation } from "../../../../utils/events";

const CLOSE_INTERVAL = 100;

const PopUpNotificationCard = ({
  title,
  description,
  icon,
  unreadLink,
  isReaded,
  onLinkClick,
  onClick,
  onClose,
  autoClose,
  onAutoClose,
  hasLink,
  footerButtons,
  onFooterConfirm,
  onFooterCancel,
  ...rest
}) => {
  //* STATES
  const [show, setShow] = React.useState(true);
  const [isClosing, setIsClosing] = React.useState(false);
  const [seconds, setSeconds] = React.useState(autoClose);
  const [stopCounter, setStopCounter] = React.useState(false);
  const [animationState, setAnimationState] = React.useState("runnig");

  const close = useCallback(() => {
    setIsClosing(true);

    setTimeout(() => {
      setShow(false);
      onAutoClose();

      setIsClosing(false);
    }, 1900);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleOnClose = () => {
    setSeconds(CLOSE_INTERVAL);
  };

  const handleOnClick = () => {
    onClick();
    handleOnClose();
  };

  const handleOnConfirm = () => {
    onFooterConfirm();
    handleOnClose();
  };

  const handleOnCancel = () => {
    onFooterCancel();
    handleOnClose();
  };
  const handleOnLinkClick = () => {
    onLinkClick();
    handleOnClose();
  };

  React.useEffect(() => {
    const interval = setInterval(() => {
      if (!stopCounter) {
        if (seconds > 0) setSeconds(seconds - CLOSE_INTERVAL);
      }
    }, CLOSE_INTERVAL);
    if (seconds === 0) close();
    return () => clearInterval(interval);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [stopCounter, seconds]);

  return (
    show && (
      <Card
        isReaded={isReaded}
        show={show}
        isClosing={isClosing}
        onMouseOver={() => {
          setStopCounter(true);
          setAnimationState("paused");
        }}
        onMouseLeave={() => {
          setStopCounter(false);
          setAnimationState("running");
        }}
        onClick={handleOnClick}
        {...rest}
      >
        <NotificationIcon iconName={icon} />
        <Title maxLines={2}>{title}</Title>
        <Description maxLines={4}>{description}</Description>

        {unreadLink && hasLink && (
          <UnreadLink onClick={e => stopPropagation(e, handleOnLinkClick)}>
            {unreadLink}
          </UnreadLink>
        )}
        <Close
          onClick={e => {
            e.stopPropagation();
            handleOnClose();
            onClose();
          }}
        >
          <Icon iconName="close" />
        </Close>
        {seconds > 0 && (
          <ProgressBarContainer>
            <ProgressBar
              animationStatus={animationState}
              animationTime={autoClose / 1000}
            />
          </ProgressBarContainer>
        )}
        {footerButtons && (
          <FooterButtons
            className="notification-footer-buttons"
            onClick={handleOnConfirm}
            onCancel={handleOnCancel}
            {...footerButtons}
          />
        )}
      </Card>
    )
  );
};

PopUpNotificationCard.propTypes = NotificationCardPropTypes;

PopUpNotificationCard.defaultProps = NotificationCardDefaultProps;

export { PopUpNotificationCard, NotificationCardDefaultProps };
