import React from "react";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

const MyLoader = props => {
  const { colors } = React.useContext(ThemeContext);
  return (
    <ContentLoader
      speed={2}
      width={300}
      height={215}
      viewBox="0 0 300 215"
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
      {...props}
    >
      <rect x="0" y="0" rx="8" ry="8" width="300" height="168" />
      <rect x="0" y="180" rx="7" ry="7" width="180" height="12" />
      <rect x="0" y="201" rx="8" ry="8" width="100" height="12" />
    </ContentLoader>
  );
};

export default MyLoader;
