import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { ROOM_VIEW_TYPE } from "~/shared/utils/types";
import GroupList from "~/modules/signed/modules/cms/components/lists/GroupList";
import {
  Scroll,
  Container,
  Form,
  Fieldset,
  FieldsetTitle,
  Dropdown,
} from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function NetworkRoomVisibility({
  selectedGroups,
  setSelectedGroups,
  viewType,
  setViewType,
}) {
  const { translate } = useTranslation();

  const viewTypes = [
    {
      id: 1,
      name: translate("entities.networkRooms.viewTypes.public"),
      value: ROOM_VIEW_TYPE.PUBLIC,
    },
    {
      id: 2,
      name: translate("entities.networkRooms.viewTypes.private"),
      value: ROOM_VIEW_TYPE.PRIVATE,
    },
    {
      id: 3,
      name: translate("entities.networkRooms.viewTypes.hidden"),
      value: ROOM_VIEW_TYPE.HIDE,
    },
  ];

  function convertViewType() {
    switch (viewType) {
      case ROOM_VIEW_TYPE.PUBLIC:
        return translate("entities.networkRooms.viewTypes.public");

      case ROOM_VIEW_TYPE.PRIVATE:
        return translate("entities.networkRooms.viewTypes.private");

      case ROOM_VIEW_TYPE.HIDE:
        return translate("entities.networkRooms.viewTypes.hidden");

      default:
        return null;
    }
  }

  function handleOnChange(value) {
    setViewType(value);
  }

  return (
    <Scroll>
      <Container>
        <Form>
          <Fieldset>
            <FieldsetTitle>
              {translate("entities.broadcastRooms.roomVisibility")}
            </FieldsetTitle>
            <Dropdown
              name="view_type"
              options={viewTypes}
              noRegistration
              initialValue={convertViewType()}
              onChange={handleOnChange}
            />
          </Fieldset>
        </Form>

        <GroupList
          style={{ marginTop: 16, minWidth: 350 }}
          selectedGroups={selectedGroups}
          setSelectedGroups={setSelectedGroups}
        />
      </Container>
    </Scroll>
  );
}

NetworkRoomVisibility.propTypes = {
  selectedGroups: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedGroups: PropTypes.func.isRequired,
  viewType: PropTypes.string.isRequired,
  setViewType: PropTypes.func.isRequired,
};

export default NetworkRoomVisibility;
