import api from "~/shared/services/api";

export const getMaterialResume = ({ startDate, endDate, materialId }) =>
  Promise.resolve(
    api.get("/admin/downloadable_material_log/export_users", {
      params: {
        start_date: startDate,
        end_date: endDate,
        downloadable_material_id: materialId,
      },
    })
  );
