import React from "react";
import PropTypes from "prop-types";

import { dateSelectPropTypes } from "../../organisms";

//* CUSTOM IMPORTS
import { Box } from "../../atoms";
import {
  Table,
  Pagination,
  paginationPropTypes,
  tablePropTypes,
  exportSheetsButtonPropTypes,
  dashboardReportCardPropTypes,
} from "../../molecules";

import {
  Container,
  Header,
  DateSelect,
  DownloadButton,
  Content,
  ReportCard,
  SearchBar,
} from "./styles";

const accessDetailsTemplatePropTypes = {
  /** Control the left card data, this is an <b>DashboardReportCard</b> component.
   * If you want too see all props you can go to <b>Molecules/DashboardReportCard</b>
   */
  cardData: PropTypes.shape(dashboardReportCardPropTypes).isRequired,

  tableData: PropTypes.shape(tablePropTypes).isRequired,

  /** If this props is implemented, add an <b>Pagination</b> component at bottom of the
   * <b>Table</b>
   */
  tablePagination: PropTypes.shape(paginationPropTypes),

  /** The date interval for the <b>DateSelect</b> component */
  dateInterval: PropTypes.shape({
    startDate: PropTypes.string,
    endDate: PropTypes.string,
  }).isRequired,

  /** Callback that is triggered when user select an date. Return the stardDate
   * and the endDate of the selected date.
   *
   * Return Example: ({startDate, endDate}) => {}
   */
  onDateSelect: dateSelectPropTypes.onSelect,

  /** Callback that is triggered when user change the value of the <b>Searchbar</b>,
   *
   * return the value of the input. (value) => {}
   */
  onSearch: PropTypes.func,

  /** If TRUE, render an spinner inside the <b>Searchbar</b> */
  isSearching: PropTypes.bool,

  searchBarPlaceholder: PropTypes.string,

  /** Render an <b>ExportSheetsButton</b> at rigth top of this template if this
   * props ahs some data
   */
  downloadButtonData: PropTypes.shape(exportSheetsButtonPropTypes).isRequired,

  /** If true render a searchBar in the template - default is true */
  hasSearchBar: PropTypes.bool,

  /** If true render a dateSelect component in the template - default is true */

  hasDateSelect: PropTypes.bool,
};

const accessDetailsTemplateDefaultProps = {
  onDateSelect: () => {},
  onSearch: () => {},
  isSearching: false,
  searchBarPlaceholder: null,
  tablePagination: null,
  hasSearchBar: true,
  hasDateSelect: true,
};

/** This tempalte is commonly used in dashboard pages */
function AccessDetailsTemplate({
  dateInterval,
  onDateSelect,
  onSearch,
  isSearching,
  searchBarPlaceholder,
  cardData,
  tableData,
  tablePagination,
  downloadButtonData,
  hasSearchBar,
  hasDateSelect,
}) {
  return (
    <Container>
      <Header>
        {hasSearchBar ? (
          <SearchBar
            isSearching={isSearching}
            placeholder={searchBarPlaceholder}
            onChange={search => onSearch(search)}
          />
        ) : (
          <></>
        )}

        <div style={{ display: "flex", alignItems: "center" }}>
          {hasDateSelect ? (
            <DateSelect
              initialValue={dateInterval}
              onSelect={value => onDateSelect(value)}
            />
          ) : (
            <></>
          )}
          {downloadButtonData && <DownloadButton {...downloadButtonData} />}
        </div>
      </Header>

      <Content>
        <ReportCard {...cardData} reportItems={cardData.data} />

        <Box style={{ flex: 1 }}>
          <Table
            {...tableData}
            loaderListSize={6}
            variant="withLines"
            containerVariant="contained"
            size="large"
          />

          {tablePagination && (
            <Pagination {...tablePagination} style={{ marginTop: 20 }} />
          )}
        </Box>
      </Content>
    </Container>
  );
}

AccessDetailsTemplate.propTypes = accessDetailsTemplatePropTypes;

AccessDetailsTemplate.defaultProps = accessDetailsTemplateDefaultProps;

export {
  AccessDetailsTemplate,
  accessDetailsTemplatePropTypes,
  accessDetailsTemplateDefaultProps,
};
