import React from "react";
import PropTypes from "prop-types";

//* APIs
import { resetUserScore } from "../../../../apis/gamification";

//* CUSTOM IMPORTS
import { Box, Typography } from "~/shared/components/atoms";
import { DialogModal } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminGamification } from "../../hooks";
import { GamificationInfos } from "~/modules/signed/components/gamification/GamificationInfos";

const resetUserScoreModalPropTypes = {
  userRanking: PropTypes.shape({
    position: PropTypes.number,
    avatar: PropTypes.string,
    name: PropTypes.name,
    points: PropTypes.number,
    user_id: PropTypes.number,
  }).isRequired,

  onClose: PropTypes.func.isRequired,
};

const ResetUserScoreModal = ({ userRanking, onClose, ...rest }) => {
  const { translate } = useTranslation();
  const { updateUserRankingScore } = useAdminGamification();

  //* STATES
  const [isReseting, setIsReseting] = React.useState(false);

  //* FUNCTIONS
  const handleOnResetUserScore = async () => {
    const userId = userRanking.user_id;
    setIsReseting(true);

    await resetUserScore(userId).then(() => {
      setIsReseting(false);
      updateUserRankingScore(0, userId);
      onClose();
    });
  };

  return (
    <DialogModal
      {...rest}
      variant="error"
      onClose={onClose}
      title={translate("entities.gamification.resetScore")}
      confirmSecurity
      confirmButton={translate("common.reset")}
      onConfirm={handleOnResetUserScore}
      isLoading={isReseting}
    >
      <Box marginTop="20px">
        <GamificationInfos
          position={userRanking.position}
          points={userRanking.points}
          userName={userRanking.name}
          avatar={{ src: userRanking.avatar, size: 48 }}
        />

        <Typography style={{ margin: "16px 0" }}>
          {translate("entities.gamification.resetUserScoreMessage")}
        </Typography>
      </Box>
    </DialogModal>
  );
};

ResetUserScoreModal.propTypes = resetUserScoreModalPropTypes;

export { ResetUserScoreModal };
