import React from "react";

// CUSTOM IMPORTS
import { Container, NotificationsRoomForm, NotificationsList } from "./styles";

export default function Notifications() {
  const [isFormVisible, setIsFormVisible] = React.useState(false);
  const [notification, setNotification] = React.useState(null);

  function showForm(n) {
    setIsFormVisible(false);
    setNotification(n);

    setTimeout(() => {
      setIsFormVisible(true);
    }, 200);
  }

  function closeForm() {
    setNotification(null);
    setIsFormVisible(false);
  }

  const isTheSameForm = n => {
    if (!n || !notification) return false;

    if (n.id === notification.id) return true;
  };

  const handleOnEditNotification = n => {
    if (isFormVisible && isTheSameForm(n)) {
      closeForm();
    } else {
      showForm(n);
    }
  };

  const handleOnAddNotification = () => {
    if (isFormVisible && !notification) {
      closeForm();
    } else {
      showForm(null);
    }
  };

  return (
    <Container>
      <NotificationsList
        isAddButtonSelected={isFormVisible && !notification}
        selectedNotification={notification && notification.id}
        onAddButton={handleOnAddNotification}
        onDelete={closeForm}
        onEdit={n => handleOnEditNotification(n)}
      />

      {isFormVisible && (
        <NotificationsRoomForm
          notification={notification}
          onClose={closeForm}
          editMode={!!notification}
        />
      )}
    </Container>
  );
}
