import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Icon, iconPropTypes } from "../../../atoms";
import {
  Container,
  Title,
  Description,
  Times,
  Time,
  Line,
  Points,
} from "./styles";

const logCellPropTypes = {
  /** The name for the render icon, see all options in <b>shared/utils/icon</b>. */
  iconName: iconPropTypes.iconName,
  /** The color of <b>Icon</b> background. */
  iconColor: iconPropTypes.backgroundColor,
  /** An string that will be renfder at right of this component. */
  time: PropTypes.string,
  /** An string that will be renfder at right of this component. */
  date: PropTypes.string,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  points: PropTypes.string,
  /** If <b>true</b> remove the default line  below the <b>Icon</b> */
  noLine: PropTypes.bool,
};

const logCellDefaultProps = {
  noLine: false,
  points: null,
  date: null,
  time: null,
  iconColor: null,
  iconName: null,
};

const LogCell = ({
  iconName,
  iconColor,
  time,
  date,
  title,
  description,
  noLine,
  points,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);

  return (
    <Container {...rest}>
      <div className="activity-cell-left">
        <Icon
          iconName={iconName}
          variant="contained"
          backgroundColor={iconColor}
          color={colors.surface}
        />

        {!noLine && <Line />}
      </div>

      {points && <Points className="log-cell-points">{points}</Points>}

      <div className="activity-cell-rigth">
        <Title>{title}</Title>

        <Description>{description}</Description>
      </div>

      <Times className="log-cell-times">
        <Time>{date}</Time>
        <Time>{time}</Time>
      </Times>
    </Container>
  );
};

LogCell.propTypes = logCellPropTypes;

LogCell.defaultProps = logCellDefaultProps;

export { LogCell, logCellPropTypes, logCellDefaultProps };
