import api from "~/shared/services/api";

export const getNotifications = ({ page }) =>
  Promise.resolve(
    api.get("/notifications", {
      params: {
        page,
      },
    })
  );

export const deleteNotification = ({ id }) =>
  Promise.resolve(api.delete(`/notifications/${id}`));

export const readAllNotifications = ({ action }) =>
  Promise.resolve(api.post(`/notifications/set/all?action=${action}`));

export const readNotification = ({ id, action }) =>
  Promise.resolve(api.post(`/notifications/set/${id}?action=${action}`));
