import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Partners, PartnerLogo } from "./styles";
import { isListValid } from "~/shared/utils/validations";
import { HoverWrapper } from "~/shared/components/atoms";
import { openMarketRoom } from "~/shared/routes/navigation";

function PartnerLogoViewList({ className, partners }) {
  return (
    isListValid(partners) && (
      <Container className={className}>
        <h4 className="partner-list-title">Sessão organizada por:</h4>

        <Partners>
          {partners.map(partner => (
            <HoverWrapper
              key={partner.id}
              onClick={() => openMarketRoom(partner.id, partner.title)}
            >
              <PartnerLogo src={partner.logo} size={70} />
            </HoverWrapper>
          ))}
        </Partners>
      </Container>
    )
  );
}

PartnerLogoViewList.propTypes = {
  partners: PropTypes.arrayOf(PropTypes.object).isRequired,
  className: PropTypes.string,
};

PartnerLogoViewList.defaultProps = {
  className: "partner-logo-list",
};

export default PartnerLogoViewList;
