/* eslint-disable array-callback-return */
import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import {
  Typography,
  Box,
  tagPropTypes,
} from "../../../../../../../../shared/components/atoms";
import { TagGroup } from "../../../../../../../../shared/components/molecules";
import { listDropdownAttributes } from "~/modules/signed/modules/client/apis/cadasterItems/dropdownAttributes";
import { Tooltip } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

const dynamicTagsPropTypes = {
  title: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  refCode: PropTypes.string.isRequired,
  max: PropTypes.number.isRequired,
  initialData: PropTypes.arrayOf(PropTypes.shape(tagPropTypes)),
};

const dynamicTagsDefaultProps = {
  initialData: [],
};

const DynamicTags = ({ title, name, refCode, initialData, max }) => {
  const { colors } = React.useContext(ThemeContext);

  //* STATES
  const [value, setValue] = React.useState();
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [tags, setTags] = React.useState([]);
  const [selectedTags, setSelectedTags] = React.useState(initialData || []);
  const [error, setError] = React.useState(null);

  //* REFS
  const inputRef = React.useRef(null);

  //* FUNCTIONS
  const { translate } = useTranslation();
  const {
    fieldName,
    error: registrationError,
    defaultValue,
    registerField,
  } = useField(name);

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, registerField]);

  React.useMemo(() => setError(registrationError), [registrationError]);

  React.useEffect(() => {
    const fetch = async () => {
      await listDropdownAttributes({ attribute_ref: refCode }).then(
        response => {
          setTags(response.data);

          if (defaultValue) {
            if (defaultValue.length > 0) {
              let string = "";

              /** Add the selectedTags that is in defaultValue only if the tags
               * are inside the tags array
               */
              setSelectedTags(
                defaultValue.filter(v => response.data.some(d => d.id === v))
              );

              defaultValue.map((v, index) => {
                string += `${String(v)}${
                  index !== defaultValue.length - 1 ? "," : ""
                }`;
              });

              setValue(string);
            }
          }

          setIsLoaded(true);
        }
      );
    };

    fetch();
  }, [defaultValue, refCode]);

  const handleSelectTag = tagId => {
    let newArray = [];
    setError(null);

    if (selectedTags.includes(tagId)) {
      newArray = selectedTags.filter(id => id !== tagId);
    } else {
      if (selectedTags.length === max) {
        setError(
          `${translate("components.dynamicTags.maxWarging.part1")} ${max}.`
        );
        return;
      }

      newArray = [...selectedTags, tagId];
    }

    setSelectedTags(newArray);
    setValue(newArray);
  };

  return (
    <>
      {title && (
        <Box
          style={{ marginBottom: 10, display: "flex", alignItems: "center" }}
        >
          <Typography className="small normal bold uppercase">
            {title}
          </Typography>

          {error && (
            <Tooltip
              message={error}
              tipColor={colors.redWrong}
              icon={{ iconName: "error" }}
            />
          )}
        </Box>
      )}

      <TagGroup
        isLoading={!isLoaded}
        hoverable
        wrap
        size="large"
        variant="outlined"
        borderRadius={4}
        tags={tags.map(tag => ({
          id: tag.id,
          name: tag.title,
          isSelected: selectedTags.includes(tag.id),
          onClick: () => handleSelectTag(tag.id),
        }))}
      />

      <input
        style={{ display: "none" }}
        name={name}
        value={value}
        ref={inputRef}
      />
    </>
  );
};

DynamicTags.propTypes = dynamicTagsPropTypes;
DynamicTags.defaultProps = dynamicTagsDefaultProps;

export { DynamicTags };
