/* eslint-disable consistent-return */
import styled, { css } from "styled-components";

export const Container = styled.div`
  width: 100%;

  ${props =>
    props.index &&
    css`
      position: relative;
      z-index: ${props.index};
    `}

  ${props => {
    switch (props.variant) {
      case "error":
        return css`
          .input-container-title,
          .input-container-icon {
            color: ${props.theme.colors.redWrong};
          }
          .input-container {
            border: 2px solid ${props.theme.colors.redWrong};
          }

          .input-container-icon {
            svg {
              color: ${props.theme.colors.redWrong};
              fill: ${props.theme.colors.redWrong};
            }
          }
        `;

      case "focus":
        return css`
          .input-container-title,
          .input-container-icon {
            color: ${props.theme.colors.accent};
          }

          .input-container-icon {
            svg {
              color: ${props.theme.colors.accent};
              fill: ${props.theme.colors.accent};
            }
          }

          .input-container {
            border: 2px solid ${props.theme.colors.accent};
          }
        `;

      case "filled":
        return css`
          .input-container-title {
            color: ${props.theme.colors.accent};
          }

          .input-container-icon {
            svg {
              color: ${props.theme.colors.accent};
              fill: ${props.theme.colors.accent};
            }
          }
        `;

      default:
        break;
    }
  }}
`;

export const TitleContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: ${props => (!props.tip ? "10px" : "4px")};
`;

export const InputArea = styled.div`
  min-height: 48px;
  display: flex;
  align-items: center;
  width: 100%;
  background: ${props => props.theme.colors.surface};
  border-radius: 4px;
  box-shadow: 0 2px 5px ${props => props.theme.colors.shadow};
  padding: 0 16px;
  border: 2px solid ${props => props.theme.colors.surface};

  input,
  textarea {
    max-width: 100%;
    flex: 1;
    background: none;
    border: none;
    color: ${props => props.theme.colors.normal};

    &::placeholder {
      color: ${props => props.theme.colors.opaque};
    }

    &:-webkit-autofill {
      -webkit-box-shadow: 0 0 0 30px ${props => props.theme.colors.surface}
        inset;
      -webkit-text-fill-color: ${props => props.theme.colors.strong};
    }
  }
`;

export const IconContainer = styled.div`
  margin-right: 12px;

  svg {
    height: 24px;
    width: 24px;
    fill: ${props => props.theme.colors.opaque};
    color: ${props => props.theme.colors.opaque};
  }
`;

export const Title = styled.h4`
  margin: 0;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: ${props => props.theme.colors.normal};
  text-align: left;

  span {
    margin-left: 2px;
    font-weight: bold;
    font-size: 14px;
    color: ${props => props.theme.colors.redWrong};
  }
`;
