import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  createNetworkRoom,
  updateNetworkRoom,
  clearNetworkRoomForm,
} from "~/modules/signed/modules/cms/modules/admin/store/networkRooms/actions";

// CUSTOM IMPORTS
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { SwipeTabs } from "~/shared/components/molecules";
import { isDataValid, isListValid } from "~/shared/utils/validations";
import { openAdminNetworkRooms } from "~/shared/routes/navigation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { Footer } from "./styles";

// TAB FRAGMENTS
import InformationTab from "./components/InformationTab";
import SponsorshipTab from "./components/SponsorshipTab";
import VisibilityTab from "./components/VisibilityTab";

import { useTranslation } from "~/shared/hooks/translation";

function NetworkRoomForm({ networkRoom }) {
  const { translate } = useTranslation();

  // REDUCER
  const dispatch = useDispatch();
  const networkRoomReducer = useSelector(state => state.adminNetworkRooms);
  const { list } = networkRoomReducer && networkRoomReducer;
  const { isCreating, isUpdating } = list;

  // STATES
  const editMode = isDataValid(networkRoom);
  const [showSaveModal, setShowSaveModal] = React.useState(false);
  const [formData, setFormData] = React.useState(null);
  const [formInitialData, setFormInitialData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);

  const [selectedTags, setSelectedTags] = React.useState([]);
  const [selectedPartners, setSelectedPartners] = React.useState([]);
  const [selectedGroups, setSelectedGroups] = React.useState([]);
  const [viewType, setViewType] = React.useState("public");

  // FUNCTIONS
  const { dTranslate } = useDynamicTranslation();

  React.useEffect(() => {
    if (editMode) {
      const { room } = networkRoom;
      setFormInitialData({
        ...networkRoom,
        ...networkRoom.room,
      });

      setViewType(room.view_type);

      // Fill selectedTags
      if (isListValid(room.tags)) {
        const tagIds = room.tags.map(tag => tag.id);
        setSelectedTags(tagIds);
      }

      // Fill selectedGroups
      if (isListValid(networkRoom.groups)) {
        const groupIds = networkRoom.groups.map(group => group.id);
        setSelectedGroups(groupIds);
      }

      // Fill selectedPartners
      if (isListValid(room.tags)) {
        const partnerIds = room.partners.map(partner => partner.id);
        setSelectedPartners(partnerIds);
      }
    }

    return () => {
      dispatch(clearNetworkRoomForm());
    };
  }, [dispatch, editMode, networkRoom]);

  // If Craete API status is finished return to list page
  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH")
      openAdminNetworkRooms();
  }, [isCreating, isUpdating]);

  function dispatchSave() {
    const formattedNetworkRoom = {
      ...formData,
      view_type: viewType,
      partnerIds: selectedPartners,
      tagIds: selectedTags,
      groupIds: selectedGroups,
    };

    if (!editMode) {
      dispatch(createNetworkRoom(formattedNetworkRoom));
    } else {
      dispatch(updateNetworkRoom(formattedNetworkRoom, networkRoom.id));
    }
  }

  // TABS
  const tabs = [
    {
      id: 1,
      name: translate("entities.tabs.broadcastTabsInformation"),
      error: infoTabError,
    },
    { id: 2, name: translate("entities.tabs.broadcastTabsSponsorship") },
    { id: 3, name: translate("entities.tabs.broadcastTabsRoomVisibility") },
  ];

  return (
    <>
      <Form
        id="networkRoom-form"
        onSubmit={data => {
          setFormData(data);
          setShowSaveModal(true);
        }}
        onError={() => setInfoTabError(true)}
        initialData={formInitialData}
        yupValidation={{
          title: Yup.string().required(
            translate("entities.networkRooms.warnings.roomName")
          ),
          video_call_url: Yup.string()
            .url(translate("entities.networkRooms.warnings.roomURL"))
            .required(translate("entities.networkRooms.warnings.linkRequired")),
        }}
      >
        <SwipeTabs size="small" alignment="left" tabs={tabs}>
          <InformationTab
            networkRoom={networkRoom}
            selectedTags={selectedTags}
            setSelectedTags={setSelectedTags}
          />

          <SponsorshipTab
            selectedPartners={selectedPartners}
            setSelectedPartners={setSelectedPartners}
          />

          <VisibilityTab
            selectedGroups={selectedGroups}
            setSelectedGroups={setSelectedGroups}
            viewType={viewType}
            setViewType={setViewType}
          />
        </SwipeTabs>
      </Form>

      <Footer
        formId="networkRoom-form"
        onCancel={openAdminNetworkRooms}
        onConfirm={dispatchSave}
        isLoading={isUpdating === "PENDING" || isCreating === "PENDING"}
        entity={translate("entities.networkRooms.singular")}
        entityName={formData && dTranslate(formData.title)}
        showModal={showSaveModal}
        modalType={editMode ? "update" : "create"}
      />
    </>
  );
}

NetworkRoomForm.propTypes = {
  networkRoom: PropTypes.object,
};

NetworkRoomForm.defaultProps = {
  networkRoom: null,
};

export default NetworkRoomForm;
