import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Icon, Box, Line, avatarPropTypes } from "../../atoms";
import { infosPropTypes } from "../../molecules";
import { Container, Infos, Button, CellList } from "./styles";

const infosSelectPropTypes = {
  /** An array of items to be rendered when this component <b>isOpened</b>.
   * This array will auto remove the selectedItem based on it is <b>id</b>.
   */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      ...infosPropTypes,
    })
  ).isRequired,
  /** The item that is current selected */
  selectedItem: PropTypes.shape({
    id: PropTypes.number,
    ...infosPropTypes,
  }).isRequired,
  /** Callback when user click into an <b>InfosButton</b> inside the list. Return the
   * itemData.
   *
   * Example: (item) => {}
   */
  onItemClick: PropTypes.func,
  /** Controls when the component is open. Default is <b>false</b>. */
  isOpened: PropTypes.bool,
  /** Callback when user click into the selectedItem button. */
  onOpen: PropTypes.func,
};

const infosSelectDefaultProps = {
  onItemClick: () => {},
  onOpen: () => {},
  isOpened: false,
};

const InfosButton = ({
  withArrows,
  title,
  subTitle,
  avatar,
  disableLine,
  ...rest
}) => (
  <>
    <Button variant="text" {...rest}>
      <Box display="flex" alignItems="center">
        <Infos
          avatar={avatar}
          title={title}
          subTitle={subTitle}
          titleStyles="opaque  small left bold"
          subTitleStyles="bold left strong subTitle"
        />

        {withArrows && (
          <Box>
            <Icon iconName="arrowUp" />
            <Icon iconName="arrowDown" />
          </Box>
        )}
      </Box>
    </Button>

    {!disableLine && (
      <Line style={{ width: "calc(100% - 16px)", margin: "0 auto" }} />
    )}
  </>
);

InfosButton.propTypes = {
  withArrows: PropTypes.bool,
  disableLine: PropTypes.bool,
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  avatar: PropTypes.shape(avatarPropTypes).isRequired,
};

InfosButton.defaultProps = {
  withArrows: false,
  disableLine: false,
};

/** This is an Select of <b>Infos</b> component. */
const InfosSelect = ({
  data,
  selectedItem,
  onItemClick,
  isOpened,
  onOpen,
  ...rest
}) => {
  const filteredData = React.useMemo(
    () => data.filter(item => item.id !== selectedItem.id),
    [data, selectedItem.id]
  );

  return (
    <Container {...rest}>
      <>
        <InfosButton
          {...selectedItem}
          className="infos-select-selected-item"
          withArrows={data.length > 1}
          onClick={data.length > 1 && onOpen}
          disableLine={!isOpened}
        />
      </>

      {isOpened && (
        <CellList
          orientation="column"
          withScroll
          listStyle={{ maxHeight: 200 }}
          isLoaded
          data={filteredData}
          cellStyle={{ width: "100%" }}
          renderCell={(infos, index) => (
            <InfosButton
              {...infos}
              onClick={() => onItemClick(infos)}
              disableLine={index === filteredData.length - 1}
            />
          )}
        />
      )}
    </Container>
  );
};

InfosSelect.propTypes = infosSelectPropTypes;

InfosSelect.defaultProps = infosSelectDefaultProps;

export { InfosSelect, infosSelectPropTypes, infosSelectDefaultProps };
