import styled from "styled-components";

//* CUSTOM IMPORTS
import { leftIn, leftOut } from "~/shared/styles/animations";
import { layers, medias } from "~/shared/styles";
import { ChatList } from "~/shared/components/organisms";

export const Container = styled(ChatList)`
  position: fixed;
  right: 0;
  top: 80px;
  z-index: ${layers.chat};
  height: calc(100vh - 80px);
  max-width: 430px;
  width: 100%;
  border-radius: 0;
  animation: ${props => (props.isOpen ? leftIn : leftOut)} 0.3s ease-in-out;
  visibility: ${props => (props.isOpen ? "visible" : "hidden")};
  transition: visibility 0.3s ease-in-out;

  #base-list {
    visibility: ${props => (props.isOpen ? "visible" : "hidden")};
  }

  @media (max-width: ${medias.largeTablet}) {
    top: 60px;
    height: calc(100vh - 60px);
  }
`;
