import api, { socketApi } from "~/shared/services/api";

export const listQuestions = ({ roomId, page }) =>
  Promise.resolve(
    api.get(`/question?broadcastRoomId=${roomId}`, {
      params: {
        page,
      },
    })
  );

export const selectQuestion = ({ questionId }) =>
  Promise.resolve(socketApi.put(`/question/show/${questionId}`));
