import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listCertificatesSuccessful,
  listCertificatesFailed,
  showCertificateSuccessful,
  showCertificateFailed,
  createCertificateSuccessful,
  createCertificateFailed,
  deleteCertificateSuccessful,
  deleteCertificateFailed,
  updateCertificateSuccessful,
  updateCertificateFailed,
  clearEvents,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/admin/certificate");

    if (validResponseStatus(response.status)) {
      const certificates = response.data;

      yield put(listCertificatesSuccessful({ certificates }));
    }
  } catch (err) {
    yield put(listCertificatesFailed());
  }
}

// SHOW
export function* show({ payload }) {
  try {
    const { certificateId } = payload;

    const response = yield call(api.get, `/admin/certificate/${certificateId}`);

    if (validResponseStatus(response.status)) {
      const certificate = response.data;

      yield put(showCertificateSuccessful({ certificate }));
    }
  } catch (err) {
    yield put(showCertificateFailed());
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { certificateData } = payload;

    const response = yield call(
      api.post,
      "/admin/certificate",
      certificateData
    );

    if (validResponseStatus(response.status)) {
      const certificate = response.data;

      yield put(createCertificateSuccessful({ certificate }));
    }

    yield put(clearEvents());
  } catch (err) {
    yield put(createCertificateFailed());
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { certificateId, certificateData } = payload;

    const response = yield call(
      api.put,
      `/admin/certificate/${certificateId}`,
      certificateData
    );

    if (validResponseStatus(response.status)) {
      const certificate = response.data;

      yield put(updateCertificateSuccessful({ certificate }));
    }

    yield put(clearEvents());
  } catch (err) {
    yield put(updateCertificateFailed());
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { certificateId } = payload;

    const response = yield call(
      api.delete,
      `/admin/certificate/${certificateId}`
    );

    if (validResponseStatus(response.status))
      yield put(deleteCertificateSuccessful({ certificateId }));

    yield put(clearEvents());
  } catch (err) {
    yield put(deleteCertificateFailed());
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.SHOW.REQUEST, show),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
]);
