import styled, { css } from "styled-components";
import { shade } from "polished";

export const Container = styled.div`
  position: relative;

  .MuiIconButton-root {
    height: ${props => props.size}px;
    width: ${props => props.size}px;
  }

  &:hover {
    svg {
      color: ${props => props.theme.colors.accent};
    }

    .icon-button-tip {
      visibility: visible;
    }
  }

  ${props => {
    const { variant, theme } = props;

    switch (variant) {
      case "surface":
        return css`
          .MuiIconButton-root {
            background-color: ${theme.colors.surface};
            box-shadow: 0 2px 6px ${theme.colors.shadow};
            &:hover {
              background-color: ${shade(0.1, theme.colors.surface)};
            }
          }
        `;

      case "primary":
        return css`
          .MuiIconButton-root {
            background-color: ${theme.colors.primary};
            &:hover {
              background-color: ${shade(0.1, theme.colors.primary)};
            }

            svg {
              color: ${theme.colors.surface};
            }
          }
        `;

      case "accent":
        return css`
          .MuiIconButton-root {
            background-color: ${theme.colors.accent};
            &:hover {
              background-color: ${shade(0.1, theme.colors.accent)};
            }

            svg {
              color: ${theme.colors.surface};
            }
          }
        `;

      default:
        break;
    }
  }}

  ${props =>
    props.color &&
    css`
      svg {
        color: ${props.color};
      }

      &:hover {
        svg {
          color: ${props.color};
        }
      }
    `}
`;
