import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  listStickyNoteComments,
  deleteStickyNoteComment,
  createStickyNoteComments,
  likeStickyNoteComment,
  dislikeStickyNoteComment,
} from "../../../../../../../../apis/stickyNotes";

//* CUSTOM IMPORTS
import { useStickyNotes } from "../../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { CommentList } from "~/shared/components/organisms";
import { Empty } from "~/shared/components/molecules";
import { API_STATUS } from "~/shared/utils/apis";
import { Icon } from "~/shared/components/atoms";
import { StickyNote, IconButton } from "./styles";
import { useSigned } from "~/modules/signed/hooks";

const stickyNotesCommentsPropTypes = {
  stickyId: PropTypes.number.isRequired,

  /** Callback when user clicks on <b>ReturnButton</b>. */
  onBack: PropTypes.func.isRequired,
};

const StickyNoteComments = ({ stickyId, onBack, ...rest }) => {
  const {
    stickyNoteComments,
    addStickyNoteComment,
    deleteStickNoteComment,
    clearStickyNoteComments,
    receiveStickyNoteComments,
    paginateStickyNoteComments,
    handleStickyNoteCommentLike,
  } = useStickyNotes();

  const { isMyUserId } = useSigned();

  //* STATES
  const [cellId, setCellId] = React.useState(null);
  const [apiStatus, setApiStatus] = React.useState(API_STATUS.IDLE);

  const { data, page, lastPage } = stickyNoteComments;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const list = async () => {
      await listStickyNoteComments({ id: stickyId, page: 1 }).then(response =>
        receiveStickyNoteComments(response.data)
      );
    };

    if (!stickyNoteComments.isLoaded) list();
  }, [receiveStickyNoteComments, stickyId, stickyNoteComments.isLoaded]);

  React.useEffect(() => {
    return () => {
      clearStickyNoteComments();
    };
  }, [clearStickyNoteComments]);

  const handleCommentsPaginate = async () => {
    if (page < lastPage) {
      setApiStatus(API_STATUS.PAGING);

      await listStickyNoteComments({
        id: stickyId,
        page: page + 1,
      }).then(response => paginateStickyNoteComments(response.data));

      setApiStatus(API_STATUS.IDLE);
    }
  };

  const handleOnComment = async comment => {
    setApiStatus(API_STATUS.CREATING);

    await createStickyNoteComments({
      comment,
      commentReplyId: null,
      id: stickyId,
    }).then(response => addStickyNoteComment(response.data));

    setApiStatus(API_STATUS.IDLE);
  };

  const handleOnDelete = async comment => {
    setApiStatus(API_STATUS.DELETING);
    setCellId(comment.id);

    await deleteStickyNoteComment({ id: comment.id }).then(() =>
      deleteStickNoteComment(comment.id, stickyId)
    );

    setCellId(null);
    setApiStatus(API_STATUS.IDLE);
  };

  const handleOnLike = async comment => {
    const { isLiked } = comment;

    handleStickyNoteCommentLike(comment.id);

    if (isLiked) {
      await dislikeStickyNoteComment({ id: comment.id });
    } else {
      await likeStickyNoteComment({ id: comment.id });
    }
  };

  return (
    <StickyNote foldPosition="bottomLeft" size={380} {...rest}>
      <IconButton onClick={onBack} title={translate("common.return")}>
        <Icon iconName="arrowBack" />
      </IconButton>
      <CommentList
        data={data.map(commentData => {
          const {
            id,
            author,
            comment,
            created_at: createdAt,
            is_liked: isLiked,
            __meta__,
          } = commentData;

          return {
            id,
            messageTitle: author.name,
            avatar: author.avatar,
            message: comment,
            createdAt,
            disableMenu: !isMyUserId(author.id),
            isLiked,
            likes: __meta__ && __meta__.total_reactions,
          };
        })}
        onDelete={handleOnDelete}
        scrollHeight={260}
        cellId={cellId}
        isLoaded={stickyNoteComments.isLoaded}
        isDeleting={apiStatus === API_STATUS.DELETING}
        isPaging={apiStatus === API_STATUS.PAGING}
        isCreating={apiStatus === API_STATUS.CREATING}
        onBottomReached={handleCommentsPaginate}
        onComment={handleOnComment}
        onLike={handleOnLike}
        chatInput={{
          placeholder: translate("common.writeMessage"),
        }}
        emptyComponent={
          <Empty
            message={translate("entities.stickyNotes.comments.empty.message")}
            description={translate(
              "entities.stickyNotes.comments.empty.description"
            )}
            icon={<Icon iconName="chat" />}
            style={{ marginBottom: 40 }}
          />
        }
      />
    </StickyNote>
  );
};

StickyNoteComments.propTypes = stickyNotesCommentsPropTypes;

export { StickyNoteComments, stickyNotesCommentsPropTypes };
