import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Tag } from "../../atoms";
import { CellList } from "../CellList";
import Loader from "../cellLoaders/CardLoader";
import { useTranslation } from "../../../hooks/translation";

const weekDayPickerPropTypes = {
  /** If <b>true,/b> render an list of <b>Loader</b> instead of the
   * default component.
   */
  isLoading: PropTypes.bool,
  /** Callback when user clicks into an Day. Return an array with the
   * new selectedDays.
   *
   * Example: (selectedDays) => {}
   */
  onChange: PropTypes.func,
  /** An array of selected days. */
  selectedDays: PropTypes.arrayOf([PropTypes.oneOf([0, 1, 2, 3, 4, 5, 6])]),
};

const weekDayPickerDefaultProps = {
  isLoading: false,
  onChange: () => {},
  selectedDays: [],
};

const getFirstLetterUppercase = string =>
  String(string)
    .substring(0, 1)
    .toUpperCase();

/** This is only a simple <b>CellList</b> that renders an array with all
 * weekDays.
 */
const DaysWeekPicker = ({ isLoading, onChange, selectedDays }) => {
  const { translate } = useTranslation();

  const weekDays = [
    { id: 0, day: getFirstLetterUppercase(translate("common.sunday")) },
    { id: 1, day: getFirstLetterUppercase(translate("common.monday")) },
    { id: 2, day: getFirstLetterUppercase(translate("common.tuesday")) },
    { id: 3, day: getFirstLetterUppercase(translate("common.wednesday")) },
    { id: 4, day: getFirstLetterUppercase(translate("common.thursday")) },
    { id: 5, day: getFirstLetterUppercase(translate("common.friday")) },
    { id: 6, day: getFirstLetterUppercase(translate("common.saturday")) },
  ];

  //* FUNCTIONS
  const handleOnDaySelect = day => {
    const isDaySelected = selectedDays.includes(day.id);

    /** If day is already in selectedDays array, remove this day from array */
    if (isDaySelected) onChange(selectedDays.filter(dayId => dayId !== day.id));
    else onChange([...selectedDays, day.id]);
  };

  return (
    <CellList
      data={weekDays}
      isLoaded={!isLoading}
      gap={8}
      renderCell={day => (
        <Tag
          hoverable
          variant="outlined"
          isSelected={selectedDays.includes(day.id)}
          format="squared"
          style={{ margin: 0, height: 32, width: 32 }}
          onClick={() => handleOnDaySelect(day)}
        >
          {day.day}
        </Tag>
      )}
      renderCellLoader={() => <Loader width={32} height={32} />}
    />
  );
};

DaysWeekPicker.propTypes = weekDayPickerPropTypes;

DaysWeekPicker.defaultProps = weekDayPickerDefaultProps;

export { DaysWeekPicker, weekDayPickerPropTypes, weekDayPickerDefaultProps };
