import styled, { css } from "styled-components";

// CUSTOM IMPORTS
import { Card as CardC, HoverWrapper } from "../../atoms";
import { SimpleMenu } from "../../molecules/SimpleMenu";
import { CRUDOptionsMenu } from "../CRUDOptionsMenu";

export const Touchable = styled(HoverWrapper)`
  position: relative;
  width: 100%;

  &:hover {
    .simple-cell-menu {
      opacity: 1;
    }
  }

  ${props => {
    switch (props.variant) {
      case "accent":
        return css`
          .simple-cell-card {
            transition: background 0.3s ease;
            background: ${props.theme.colors.accent};

            * {
              color: #fff !important;
            }
          }

          .simple-cell-menu {
            svg {
              color: #fff;
            }
          }
        `;

      default:
        break;
    }
  }}
`;

export const Card = styled(CardC)`
  max-width: 100%;
  width: 100%;
  min-height: 74px;
  padding: 20px;
`;

export const DefaultMenu = styled(CRUDOptionsMenu)`
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  transition: opacity 0.3s;
`;

export const CustomMenu = styled(SimpleMenu)`
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  transition: opacity 0.3s;
`;
