import React from "react";
import PropTypes from "prop-types";

// APIs
import {
  blockAllUserChats,
  unBlockUserChat,
} from "~/modules/signed/modules/cms/modules/admin/apis/block/users";

import { highlightControl } from "~/modules/signed/modules/cms/modules/admin/apis/highlight";

// CUSTOM IMPORTS
import { Container, Checkbox, Fieldset, Title, FieldItem } from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function ModerationTab({ user }) {
  const { translate } = useTranslation();
  // STATES
  const [isBlocked, setIsblocked] = React.useState(user.is_blocked);
  const [hasHiglight, setHasHighlight] = React.useState(user.has_highlight);

  async function blockUser() {
    blockAllUserChats({ userId: user.id }).then(() => setIsblocked(true));
  }

  async function unblockUser() {
    await unBlockUserChat({ userId: user.id }).then(() => setIsblocked(false));
  }

  function handleBlockUser() {
    if (isBlocked) {
      unblockUser();
    } else {
      blockUser();
    }
  }

  async function addHiglight() {
    await highlightControl({
      userId: user.id,
      has_highlight: true,
    }).then(() => {
      setHasHighlight(true);
    });
  }

  async function rmHiglight() {
    await highlightControl({
      userId: user.id,
      has_highlight: false,
    }).then(() => {
      setHasHighlight(false);
    });
  }

  function handleHighlight() {
    if (hasHiglight) {
      rmHiglight();
    } else {
      addHiglight();
    }
  }

  return (
    <Container>
      <Fieldset>
        <FieldItem>
          <Checkbox checked={isBlocked} onClick={handleBlockUser} />
          <Title>{translate("entities.users.blockChatUsers.blockUsers")}</Title>
        </FieldItem>
        <FieldItem>
          <Checkbox checked={hasHiglight} onClick={handleHighlight} />
          <Title>
            {translate("entities.users.messageHighlight.addMessageHighlight")}
          </Title>
        </FieldItem>
      </Fieldset>
    </Container>
  );
}

ModerationTab.propTypes = {
  user: PropTypes.object.isRequired,
};

export default ModerationTab;
