import React from "react";

//* CUSTOM IMPORTS
import { GamificationInfos } from "~/modules/signed/components";
import { useTranslation } from "~/shared/hooks/translation";
import {
  Typography,
  Icon,
  Spinner,
  HoverWrapper,
} from "~/shared/components/atoms";
import {
  Podium as PodiumItem,
  SearchBar,
  Pagination,
  Empty,
} from "~/shared/components/molecules";
import { API_STATUS } from "~/shared/utils/apis";
import { RankingTable } from "~/shared/components/organisms";
import { useGamification } from "../../../../hooks/gamification";
import {
  openBroadcastRooms,
  openUserProfile,
} from "~/shared/routes/navigation";
import { HomeTemplate } from "~/shared/components/templates";
import {
  Container,
  LeftContainer,
  RigthContainer,
  Podium,
  PodiumContainer,
} from "./styles";

function RankingContent() {
  const { userPoints } = useGamification();
  const { translate } = useTranslation();
  const {
    rankingList,
    listRanking,
    rankingApiStatus,
    podium,
    renderGamificationFrame,
  } = useGamification();

  //* STATES
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS
  React.useEffect(() => {
    listRanking(1, search);
  }, [listRanking, search]);

  const formmatedTableData = React.useMemo(
    () =>
      rankingList.data
        .filter(ranking => ranking.rank_position > 3)
        .map(ranking => ({
          ...ranking,
          frame: renderGamificationFrame(ranking.rank_position),
        })),
    [rankingList.data, renderGamificationFrame]
  );

  return (
    <HomeTemplate disableMedias>
      <Container>
        <LeftContainer className="left-container">
          <Typography className="title bold accent">
            {translate("common.ranking")}
          </Typography>

          <PodiumContainer className="podium-container">
            {podium.length > 2 ? (
              <Podium>
                <HoverWrapper
                  onClick={() => openUserProfile(podium[1].user_id)}
                >
                  <PodiumItem
                    name={podium[1].name}
                    avatar={{
                      src: podium[1].avatar,
                      size: 100,
                      frame: renderGamificationFrame(podium[1].rank_position),
                    }}
                    position={podium[1].rank_position}
                    awards={podium[1].points}
                    frame={null}
                    icon={null}
                    size={160}
                  />
                </HoverWrapper>

                <HoverWrapper
                  onClick={() => openUserProfile(podium[0].user_id)}
                >
                  <PodiumItem
                    name={podium[0].name}
                    avatar={{
                      src: podium[0].avatar,
                      size: 115,
                      frame: renderGamificationFrame(podium[0].rank_position),
                    }}
                    position={podium[0].rank_position}
                    awards={podium[0].points}
                    frame={null}
                    icon="crown"
                    size={200}
                  />
                </HoverWrapper>

                <HoverWrapper
                  onClick={() => openUserProfile(podium[2].user_id)}
                >
                  <PodiumItem
                    name={podium[2].name}
                    avatar={{
                      src: podium[2].avatar,
                      size: 100,
                      frame: renderGamificationFrame(podium[2].rank_position),
                    }}
                    position={podium[2].rank_position}
                    awards={podium[2].points}
                    frame={null}
                    icon={null}
                    size={140}
                  />
                </HoverWrapper>
              </Podium>
            ) : (
              <Spinner centered style={{ padding: 60 }} />
            )}
          </PodiumContainer>
        </LeftContainer>

        <RigthContainer className="right-container">
          <Typography className="bold subTitle" style={{ marginBottom: 10 }}>
            {translate("entities.users.all")}
          </Typography>
          <SearchBar
            className="search-bar"
            placeholder={translate("entities.users.search")}
            onChange={value => setSearch(value)}
          />
          <RankingTable
            isLoading={
              rankingApiStatus === API_STATUS.PAGING || !rankingList.isLoaded
            }
            loaderListSize={10}
            data={formmatedTableData}
            style={{ minWidth: 400 }}
            empty={{
              variant: search && "search",
              search,
              message: translate("entities.gamification.empty.message"),
              description: translate("entities.gamification.empty.description"),
              icon: <Icon iconName="gamification" />,
            }}
            onUserNameClick={ranking => openUserProfile(ranking.user_id)}
          />
          <Pagination
            currentPage={rankingList.page}
            lastPage={rankingList.lastPage}
            handlePagination={page => listRanking(page, search)}
            style={{ margin: "20px 0 30px" }}
          />
          <Typography className="bold subTitle" style={{ marginBottom: 10 }}>
            {translate("entities.gamification.myPositionInRank")}
          </Typography>

          <GamificationInfos
            position={userPoints && userPoints.rank_position}
            points={userPoints && userPoints.points}
            userName={userPoints && userPoints.name}
            avatar={{ src: userPoints && userPoints.avatar, size: 40 }}
            isLoading={!userPoints}
          />
        </RigthContainer>
      </Container>
    </HomeTemplate>
  );
}

const Ranking = () => {
  const { settings } = useGamification();
  const { translate } = useTranslation();

  if (!settings) return <Spinner centered />;

  if (!settings.show_user_ranking)
    return (
      <Empty
        message={translate("entities.gamification.rankingUnavailable.message")}
        description={translate(
          "entities.gamification.rankingUnavailable.description"
        )}
        icon={<Icon iconName="trophy" />}
        button={{
          children: translate("entities.broadcastRooms.backTo"),
          onClick: openBroadcastRooms,
        }}
        style={{ paddingBottom: 160 }}
      />
    );

  return <RankingContent />;
};

export default Ranking;
