import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, ExtraButton, Link } from "./styles";
import { isDataValid, hasHTTP } from "~/shared/utils/validations";
import { setIcon } from "~/shared/utils/icons";

function ExtraButtonViewList({ className, extraButtons, onClick }) {
  const validLink = link => {
    if (hasHTTP(link)) return link;
    return null;
  };

  return (
    isDataValid(extraButtons) && (
      <Container className={className}>
        {extraButtons.map(contactButton => (
          <Link
            key={contactButton.id}
            href={validLink(contactButton.url)}
            target="_blank"
          >
            <ExtraButton
              icon={setIcon(contactButton.type)}
              onClick={() => onClick(contactButton)}
            >
              {contactButton.label}
            </ExtraButton>
          </Link>
        ))}
      </Container>
    )
  );
}

ExtraButtonViewList.propTypes = {
  className: PropTypes.string,
  extraButtons: PropTypes.any,
  onClick: PropTypes.func,
};

ExtraButtonViewList.defaultProps = {
  className: null,
  extraButtons: null,
  onClick: () => {},
};

export default ExtraButtonViewList;
