import React, { createContext } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { useSocket } from "~/modules/signed/hooks/webSocket";

//* ACTIONS
import { receiveBrodcastRoomReaction } from "../../../store/reactions/actions";

const BrRoomReactionSocketContext = createContext(null);

const BrRoomReactionSocketProvider = ({ children }) => {
  const { subscribe, closeChannel } = useSocket();
  const dispatch = useDispatch();
  const { broadcastRoomId } = useParams();

  //* FUNCTIONS
  React.useEffect(() => {
    const channel = subscribe(`reaction:${broadcastRoomId}`);

    if (channel) {
      channel.on("receive_broadcast_reactions", reaction => {
        const { last_reaction } = reaction;

        if (last_reaction !== null)
          dispatch(receiveBrodcastRoomReaction({ reaction: last_reaction }));
      });
    }

    return () => {
      closeChannel(channel);
    };
  }, [broadcastRoomId, closeChannel, dispatch, subscribe]);

  return (
    <BrRoomReactionSocketContext.Provider value={{}}>
      {children}
    </BrRoomReactionSocketContext.Provider>
  );
};

BrRoomReactionSocketProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useBrRoomReactionSocket() {
  const context = React.useContext(BrRoomReactionSocketContext);

  if (!context)
    throw new Error(
      "useBrRoomReactionSocket must be used within BrRoomReactionSocketProvider"
    );

  return context;
}

export { BrRoomReactionSocketProvider, useBrRoomReactionSocket };
