import produce from "immer";

import types from "./types";
import partnerTypes from "../partners/types";
import { isDataValid } from "~/shared/utils/validations";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },
};

export default function adminMarketRoomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { marketRooms } = action.payload;

        if (isDataValid(marketRooms)) draft.list.data = marketRooms;

        draft.list.isLoaded = true;

        break;
      }

      // DELETE
      case partnerTypes.DELETE.SUCCESSFUL: {
        const { marketRoomId, partnerId } = action.payload;

        const auxList = draft.list.data;

        const marketRooms = auxList.find(
          marketRoom => marketRoom.id === marketRoomId
        );

        const { partners } = marketRooms;
        const filteredPartners = partners.filter(obj => obj.id !== partnerId);

        marketRooms.partners = filteredPartners;

        const marketRoomIndex = auxList.findIndex(
          marketRoom => marketRoom.id === marketRoomId
        );

        auxList[marketRoomIndex] = marketRooms;

        draft.list.data = auxList;

        break;
      }

      // REORDER CATEGORY
      case types.CATEGORY.REORDER: {
        const { reorderedList } = action.payload;
        if (isDataValid(reorderedList)) draft.list.data = reorderedList;

        break;
      }

      // CREATE CATEGORY
      case types.CATEGORY.CREATE_SUCCESSFUL: {
        const { category } = action.payload;

        if (isDataValid(category)) {
          const newCategory = {
            ...category,
            partners: [],
          };

          const { order } = category;
          const auxArray = draft.list.data;

          auxArray.splice(order - 1, 0, newCategory);

          draft.list.data = auxArray;
        }
        break;
      }

      // UPDATE CATEGORY
      case types.CATEGORY.UPDATE_SUCCESSFUL: {
        const { category, categoryId } = action.payload;

        if (isDataValid(category)) {
          const filteredArray = draft.list.data.filter(
            obj => obj.id !== categoryId
          );

          const hasUpdatedArray = draft.list.data.find(
            obj => obj.id === category.id
          );

          const auxPartners = hasUpdatedArray.partners;

          const updatedCategory = {
            ...category,
            partners: auxPartners,
          };

          const { order } = category;

          filteredArray.splice(order - 1, 0, updatedCategory);

          draft.list.data = filteredArray;
        }
        break;
      }

      // DELETE CATEGORY
      case types.CATEGORY.DELETE_SUCCESSFUL: {
        const { categoryId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== categoryId
        );

        draft.list.data = filteredArray;
        draft.list.isDeleting = "FINISH";

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isUpdating = null;
        draft.list.isDeleting = null;
        draft.list.isCreating = null;
        draft.list.isLoaded = false;
        break;
      }
      default:
    }
  });
}
