import axios from "axios";

// CUSTOM IMPORTS
import { isDataValid } from "../utils/validations";
import { showAPIErrors, showAPIMessages } from "../utils/apis";

const api = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,
});

export const socketApi = axios.create({
  baseURL: process.env.REACT_APP_SOCKET_API_URL,
});

// Middleware to all axios instance
socketApi.interceptors.response.use(
  response => {
    if (isDataValid(response.data)) {
      const { showable, msg } = response.data;

      if (!isDataValid(showable)) {
        if (isDataValid(msg)) showAPIMessages(msg);
      }
    }

    return response;
  },
  error => {
    showAPIErrors(error);
    return Promise.reject(error);
  }
);

// Middleware to all axios instance
api.interceptors.response.use(
  response => {
    if (isDataValid(response.data)) {
      const { showable, msg } = response.data;

      if (!isDataValid(showable)) {
        if (isDataValid(msg)) showAPIMessages(msg);
      }
    }

    return response;
  },
  error => {
    showAPIErrors(error);
    return Promise.reject(error);
  }
);

export default api;
