import React from "react";
import PropTypes from "prop-types";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

const draggableListPropTypes = {
  /** objects <b>ID</b> inside data array <b>NEEDS</b> to be an String */
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  isDragDisabled: PropTypes.bool,
  /** An callback to render the list cells, return the value of data[index] */
  renderCell: PropTypes.func.isRequired,

  /** An callback that is triggered when user finish the drop action,
   * return an reordered list reoderedList => {}
   */
  onDragFinish: PropTypes.func,
};

const draggableListDefaultProps = {
  onDragFinish: () => {},
  isDragDisabled: false,
};

/** The default DraggableList of the application
 *
 * DraggableList use react-beautiful-dnd see more here: https://github.com/atlassian/react-beautiful-dnd
 */
function DraggableList({ data, onDragFinish, renderCell, isDragDisabled }) {
  // FUNCTIONS
  // a little function to help us with reordering the result
  const reorder = (data, startIndex, endIndex) => {
    const result = Array.from(data);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
  };

  const onDragEnd = result => {
    // dropped outside the data
    if (!result.destination) {
      return;
    }

    const items = reorder(data, result.source.index, result.destination.index);

    onDragFinish(items);
  };

  const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    marginBottom: 16,

    // styles we need to apply on draggables
    ...draggableStyle,
  });

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="droppable">
        {(provided, snapshot) => (
          <div {...provided.droppableProps} ref={provided.innerRef}>
            {data.map((item, index) => (
              <Draggable
                isDragDisabled={isDragDisabled}
                key={item.id}
                draggableId={item.id}
                index={index}
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={getItemStyle(
                      snapshot.isDragging,
                      provided.draggableProps.style
                    )}
                  >
                    {renderCell(item, index)}
                  </div>
                )}
              </Draggable>
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </DragDropContext>
  );
}

DraggableList.propTypes = draggableListPropTypes;

DraggableList.defaultProps = draggableListDefaultProps;

export { DraggableList, draggableListPropTypes, draggableListDefaultProps };
