import api from "~/shared/services/api";

export const getVariable = ({ key }) =>
  Promise.resolve(api.get("/admin/variable", { params: { key } }));

export const getVariableUser = ({ key }) =>
  Promise.resolve(api.get("/user/variable", { params: { key } }));

export const createVariable = ({ key, value }) =>
  Promise.resolve(
    api.post("/admin/variable", {
      key,
      value,
    })
  );
