import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const textAreaPropTypes = {
  /** Enable TextArea to write  */
  enabled: PropTypes.bool,
};

const textAreaDefaultProps = {
  enabled: false,
};

/** An TextArea with dynamic size, accept jumpLines.
 *
 * TextArea use <b>react-autosize-textarea</b> lib, see Documentation here: <b>https://github.com/buildo/react-autosize-textarea</b>
 */
function TextArea({ enabled, ...rest }) {
  return <Container {...rest} disabled={!enabled} />;
}

TextArea.propTypes = textAreaPropTypes;

TextArea.defaultProps = textAreaDefaultProps;

export { TextArea, textAreaPropTypes, textAreaDefaultProps };
