import React from "react";
import PropTypes from "prop-types";

import {
  Modal,
  SpeakerPicture,
  Fieldset,
  Text,
  CustomButtonContent,
} from "./styles";
import { isDataValid } from "../../../../../shared/utils/validations";
import { TextArea } from "../../../../../shared/components/atoms";
import { useTranslation } from "../../../../../shared/hooks/translation";

function SpeakerDetailsModal({ speaker, customButton, ...rest }) {
  const { translate } = useTranslation();

  return (
    isDataValid(speaker) && (
      <Modal {...rest} title={translate("entities.speakers.profile")}>
        <main>
          <SpeakerPicture src={speaker.picture} size={125} />

          <ul>
            <Fieldset>
              <small>{translate("entities.speakers.plural")}</small>
              <Text>{speaker.name}</Text>
            </Fieldset>

            {speaker.role && (
              <Fieldset>
                <small>{translate("common.role")}</small>
                <Text>{speaker.role}</Text>
              </Fieldset>
            )}
            {speaker.company && (
              <Fieldset>
                <small>{translate("common.company")}</small>
                <Text>{speaker.company}</Text>
              </Fieldset>
            )}
          </ul>
        </main>

        {speaker.bio && (
          <Fieldset>
            <small>{translate("common.bio")}</small>
            <TextArea defaultValue={speaker.bio} />
          </Fieldset>
        )}

        {customButton && (
          <CustomButtonContent className="room-custom-button">
            {customButton}
          </CustomButtonContent>
        )}
      </Modal>
    )
  );
}

SpeakerDetailsModal.propTypes = {
  speaker: PropTypes.object,
  customButton: PropTypes.element,
};

SpeakerDetailsModal.defaultProps = {
  speaker: null,
  customButton: null,
};

export default SpeakerDetailsModal;
