import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { avatarPropTypes } from "../../../atoms";
import {
  infosDefaultProps,
  SimpleMenu,
  simpleMenuDefaultProps,
  FooterButtons,
  footerButtonsDefaultProps,
} from "../../../molecules";
import { Container, ButtonContainer, Infos } from "./styles";
import CheckListCellLoader from "../../../molecules/cellLoaders/CheckListCellLoader";

const profileConnectionCellPropTypes = {
  /** Control all props for <b>Infos</b> component. See all props into <b>molecules/Infos</b> */
  infos: PropTypes.shape(infosDefaultProps).isRequired,

  /** Control all props for <b>SimpleMenu</b> component. See all props into <b>molecules/SimpleMenu</b> */
  simpleMenu: PropTypes.shape(simpleMenuDefaultProps),

  /** Control all props for <b>FooterByuttons</b> component. See all props into <b>molecules/FooterButtons</b> */
  footerButtons: PropTypes.shape(footerButtonsDefaultProps),

  /** Callback triggered when the Card is clicked */
  onClick: PropTypes.func,

  /** if false render a <b>CheckListCellLoader<\b> */
  isDeleting: PropTypes.bool,

  /** Control all props for <b>Avatar</b> component. See all props into <b>Atoms/Avatar</b> */
  avatar: PropTypes.shape(avatarPropTypes),
};

const profileConnectionCellDefaultProps = {
  simpleMenu: null,
  footerButtons: null,
  onClick: () => {},
  isDeleting: false,
  avatar: null,
};

const ProfileConnectionCell = ({
  infos,
  simpleMenu,
  footerButtons,
  onClick,
  isDeleting,
  avatar,
  ...rest
}) => (
  <>
    {!isDeleting ? (
      <Container onClick={onClick} {...rest}>
        <Infos {...infos} avatar={avatar} />

        <ButtonContainer>
          {simpleMenu && (
            <SimpleMenu className="profile-right-button" {...simpleMenu} />
          )}

          {footerButtons && (
            <FooterButtons
              className="profile-right-button"
              {...footerButtons}
            />
          )}
        </ButtonContainer>
      </Container>
    ) : (
      <CheckListCellLoader
        avatarSize={68}
        style={{ marginRight: "160px", marginBottom: "32px" }}
      />
    )}
  </>
);

ProfileConnectionCell.propTypes = profileConnectionCellPropTypes;

ProfileConnectionCell.defaultProps = profileConnectionCellDefaultProps;

export {
  ProfileConnectionCell,
  profileConnectionCellPropTypes,
  profileConnectionCellDefaultProps,
};
