import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import {
  Typography as TypographyC,
  Tooltip as TooltipC,
} from "../../../../atoms";

export const Container = styled.div`
  display: flex;
  position: relative;

  ${props => {
    const { variant, theme } = props;

    //* Variant styles control
    switch (variant) {
      case "withLines":
        return css`
          border-bottom: ${!props.isTheLastOne &&
            `1px solid ${theme.colors.smoked}`};
        `;

      case "colored":
        return css`
          &:nth-child(even) {
            background-color: ${theme.colors.shadeSurface};
          }
        `;

      case "title":
        return css`
          .table-row-value {
            text-transform: uppercase;
            font-weight: bold;
            color: ${theme.colors.normal};
          }
        `;

      default:
        break;
    }
  }}

  ${props => {
    const { size } = props;

    //* Size styles control
    switch (size) {
      case "small":
        return css`
          .table-row-value {
            font-size: 10px;
          }
        `;

      case "regular":
        return css`
          padding: 8px;

          .table-row-value {
            font-size: 14px;
          }
        `;

      case "large":
        return css`
          padding: 24px 0;

          .table-row-value {
            font-size: 16px;
          }
        `;

      default:
        break;
    }
  }}
`;

export const Typography = styled(TypographyC)`
  cursor: ${props => (props.onClick ? "pointer" : "unset")};
  width: ${props => props.width};
  text-align: left;

  ${props =>
    !props.width &&
    css`
      flex: 1;
    `}

  ${props =>
    props.error &&
    css`
      color: ${props.theme.colors.redWrong};
      font-weight: bold;
    `}
`;

export const Tooltip = styled(TooltipC)`
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
`;
