import chatTypes from "./types";

export function receiveBrodcastRoomReaction({ reaction }) {
  return {
    type: chatTypes.channel.broadcastRoom.RECEIVE_REACTION,
    payload: { reaction },
  };
}

export function sendBroadcastRoomReaction({ broadcastRoomId, emojiType }) {
  return {
    type: chatTypes.channel.broadcastRoom.SEND_REACTION,
    payload: { broadcastRoomId, emojiType },
  };
}

export function receiveBrodcastRoomChatReaction({ reaction }) {
  return {
    type: chatTypes.channel.broadcastRoomChat.RECEIVE_REACTION,
    payload: { reaction },
  };
}

export function sendBroadcastRoomChatReaction({
  broadcastRoomId,
  messageId,
  emojiType,
}) {
  return {
    type: chatTypes.channel.broadcastRoomChat.SEND_REACTION,
    payload: { broadcastRoomId, emojiType, messageId },
  };
}

export function removeBroadcastRoomChatReaction({
  broadcastRoomId,
  messageId,
}) {
  return {
    type: chatTypes.channel.broadcastRoomChat.REMOVE_REACTION,
    payload: { broadcastRoomId, messageId },
  };
}
