import styled, { css } from "styled-components";
import { lighten } from "polished";
import PerfectScroll from "react-perfect-scrollbar";

//* CUSTOM IMPORTS
import { IconButton, TextArea, Typography } from "../../atoms";
import { ReplyMessageCard as ReplyMessageCardC } from "../ReplyMessageCard";
import { EmojiPicker as EmojiPickerC } from "../../organisms/EmojiPicker";

export const Container = styled.form`
  width: 100%;
  border-radius: 4px;
  background: ${props => props.theme.colors.surface};
  box-shadow: 0 3px 6px ${props => props.theme.colors.shadow};
  border: 2px solid transparent;
  position: relative;

  ${props =>
    props.isFocused &&
    !props.isSpam &&
    css`
      border: 2px solid ${props.theme.colors.accent};
    `}

  ${props =>
    props.isBlocked &&
    css`
      border: 2px solid ${props.theme.colors.redWrong};

      .chat-text-area {
        &::placeholder {
          color: ${props.theme.colors.redWrong};
        }
      }

      .send-message-button {
        svg {
          color: ${props.theme.colors.redWrong};
        }
      }
      .emoji-picker {
        svg {
          color: ${props.theme.colors.redWrong};
        }
      }
    `}
`;

export const EmojiPicker = styled(EmojiPickerC)`
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
`;

export const ReplyMessageScroll = styled(PerfectScroll)`
  max-height: 100px;
`;

export const ReplyMessageCard = styled(ReplyMessageCardC)`
  margin: 8px;
`;

export const InputContainer = styled.div`
  display: flex;
  align-items: center;
`;

export const InputMessage = styled(TextArea)`
  border: none;
  display: flex;
  background: none;
  color: ${props => props.theme.colors.strong};
  width: 100%;
  margin-left: 16px;
`;

export const SubmitButton = styled(IconButton)`
  svg {
    color: ${props =>
      props.isDisabled ? props.theme.colors.opaque : props.theme.colors.accent};
    height: 20px;
    width: 20px;
  }
`;

export const Limiter = styled(Typography)`
  margin-left: 8px;
  color: ${props => {
    const { variant, theme } = props;

    switch (variant) {
      case "reached":
        return theme.colors.redWrong;

      case "reaching":
        return theme.colors.yellowWarning;

      default:
        return theme.colors.smoked;
    }
  }};
`;

export const SpamLabel = styled.h2`
  font-size: 12px;
  text-align: left;
  font: normal normal bold 12px/15px Lato;
  opacity: 0.5;
  margin-bottom: 8px;
`;

export const SpamContainer = styled.div`
  ${props =>
    props.isSpam &&
    css`
      border-radius: 8px 8px 8px 0px;
      margin-left: -10px;
      margin-right: -10px;
      margin-bottom: -10px;
      background: ${lighten(0.32, props.theme.colors.accent)};
      padding: 10px;
    `}
`;
