import React from "react";
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from "react-icons/md";

// CUSTOM IMPORTS
import { propTypes } from "../propTypes";
import { CustomCarousel, Banner } from "./styles";

function DefaultCarrousal({ slides, onClick, ...rest }) {
  // FUNCTION
  const onBannerClick = url => window.open(url);

  return (
    <div {...rest}>
      <CustomCarousel
        indicators={false}
        prevIcon={<MdKeyboardArrowLeft size={32} />}
        nextIcon={<MdKeyboardArrowRight size={32} />}
      >
        {slides.map(slide => (
          <CustomCarousel.Item key={slide.id}>
            <Banner
              src={slide.image}
              onClick={() => {
                onBannerClick(slide.url);
                onClick(slide);
              }}
            />
          </CustomCarousel.Item>
        ))}
      </CustomCarousel>
    </div>
  );
}

DefaultCarrousal.propTypes = propTypes;

export default DefaultCarrousal;
