import React from "react";
import { MdHelp, MdColorLens } from "react-icons/md";
import Icon from "@mdi/react";
import { mdiDesktopMac } from "@mdi/js";
import { useParams } from "react-router-dom";

// APIs
import {
  listQuestions,
  selectQuestion,
} from "~/modules/signed/modules/client/apis/broadcastRooms/questions";
import {
  createVariable,
  getVariable,
} from "~/modules/signed/modules/cms/modules/admin/apis/variables";

// CUSTOM IMPORTS
import QuestionCell from "~/shared/components/organisms/cells/QuestionCell";
import {
  Empty,
  VirtualizedList,
  LoaderContainer,
} from "~/shared/components/molecules";
import { isArraysEquals, isDataValid } from "~/shared/utils/validations";
import CellLoader from "~/shared/components/molecules/cellLoaders/FieldsetLoader";
import { IconedButton, Typography } from "~/shared/components/atoms";
import { Container, Header, Waiting, Card, Buttons } from "./styles";
import { ColorPicker } from "~/shared/components/organisms";
import { chromaKey } from "~/shared/utils/variables";
import { useTranslation } from "~/shared/hooks/translation";

function QuestionList() {
  const { roomId } = useParams();

  //* STATES
  const [questions, setQuestions] = React.useState([]);
  const [show, setShow] = React.useState(false);
  const [initialColor, setInitialColor] = React.useState("#00f51f");
  const [pagination, setPagination] = React.useState({
    page: 1,
    lastPage: 2,
  });
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isPaging, setIsPaging] = React.useState(false);
  const [checkedQuestion, setCheckedQuestion] = React.useState(null);

  //* FUNCTIONS
  const { translate } = useTranslation();

  async function getColor() {
    await getVariable({ key: chromaKey.COLOR }).then(response => {
      if (isDataValid(response.data.value))
        setInitialColor(response.data.value);
    });
  }
  React.useEffect(() => {
    const handleSwitch = async () => {
      if (roomId)
        await listQuestions({ roomId, page: 1 }).then(response => {
          const { data } = response.data;
          const selectedQuestion = data.find(
            question => question.show_selected
          );
          if (selectedQuestion) {
            setCheckedQuestion(selectedQuestion.id);
          }
        });
    };
    handleSwitch();
  }, [roomId]);

  React.useEffect(() => {
    let lastArray = [];

    const list = async (reCalling = false) => {
      if (roomId)
        await listQuestions({ roomId, page: 1 }).then(response => {
          const { data } = response.data;
          if (reCalling) {
            /** If Arrays is not equals has new data to add */
            const hasNewData = !isArraysEquals(
              data.map(value => value.id),
              lastArray.map(value => value.id)
            );

            if (hasNewData) {
              const newQuestions = [];

              data.forEach(value => {
                /** If the lastArray do not contains an object with the same
                 * if as the value insert the value object into the newQuestions array
                 */
                if (!lastArray.some(item => item.id === value.id))
                  newQuestions.push(value);
              });

              /** Add the newQuestions at the top of the list */
              setQuestions(l => {
                const newArray = [...newQuestions, ...l];

                return newArray;
              });

              lastArray = data;
            }
          } else {
            setQuestions(data);
            lastArray = data;
          }

          setIsLoaded(true);
        });
    };

    list();

    /** Recall list question every 20s */
    setInterval(() => list(true), 20000);
  }, [roomId]);

  const handleChangeColor = async color => {
    await createVariable({ key: chromaKey.COLOR, value: color });
  };

  const onQuestionSelect = async id => {
    setCheckedQuestion(id);
    if (id === checkedQuestion) {
      setCheckedQuestion(null);
    }

    await selectQuestion({ questionId: id }).catch(() => {
      setCheckedQuestion(null);
    });
  };

  const handlePagination = async () => {
    setIsPaging(true);

    await listQuestions({ roomId, page: pagination.page + 1 }).then(
      response => {
        const { page, lastPage, data } = response.data;

        setPagination({
          page,
          lastPage,
        });

        setQuestions([...questions, ...data]);
      }
    );

    setIsPaging(false);
  };

  return (
    <Container>
      <Header>
        <Typography className="accent bold title">
          {translate("pages.questions.userQuestions")}
        </Typography>

        <Buttons>
          <IconedButton
            style={{ marginLeft: "16px" }}
            type="button"
            title="Selecionar cor"
            icon={<MdColorLens />}
            onClick={async () => {
              await getColor();
              setShow(true);
            }}
          >
            {translate("pages.questions.selectColor")}
          </IconedButton>

          <a
            className="noHoverEffects"
            href={`/questions/${roomId}/preview`}
            target="_blank"
            rel="noreferrer noopener"
          >
            <IconedButton
              style={{ marginLeft: "16px" }}
              type="button"
              title="Abrir visualização de perguntas"
              icon={<Icon path={mdiDesktopMac} />}
            >
              {translate("pages.questions.openBroadcast")}
            </IconedButton>
          </a>
        </Buttons>
      </Header>

      <Waiting>{translate("pages.questions.waitingQuestions")}</Waiting>

      <Card>
        {!isLoaded ? (
          <LoaderContainer
            cellLoader={<CellLoader />}
            size={6}
            gap={40}
            orientation="column"
            style={{ padding: 40 }}
          />
        ) : (
          <VirtualizedList
            data={questions}
            renderCell={(cell, index, measure) => {
              const { id, question, user, created_at: createdAt } = cell;

              return (
                <QuestionCell
                  onLoad={measure}
                  marker={id}
                  message={question}
                  title={user.name}
                  style={{ marginTop: 20, marginBottom: 30, padding: "0 20px" }}
                  createdAt={createdAt}
                  isChecked={id === checkedQuestion}
                  onSwitchClick={() => onQuestionSelect(id)}
                />
              );
            }}
            emptyComponent={
              <Empty
                icon={<MdHelp />}
                message={translate("pages.questions.noQuestions")}
                description={translate(
                  "pages.questions.noQuestionsDescription"
                )}
              />
            }
            onBottomReached={handlePagination}
            disableScrollListener={pagination.page >= pagination.lastPage}
            footerComponent={
              isPaging && (
                <LoaderContainer
                  cellLoader={<CellLoader />}
                  size={3}
                  gap={40}
                  orientation="column"
                  style={{ padding: 20 }}
                />
              )
            }
          />
        )}
      </Card>

      <ColorPicker
        show={show}
        onClose={() => setShow(false)}
        colorInput={initialColor || "#00f51f"}
        form={{
          onSubmit: ({ color }) => {
            handleChangeColor(color);
            setShow(false);
          },
        }}
        footerButtons={{ onCancel: () => setShow(false) }}
      />
    </Container>
  );
}

export default QuestionList;
