import React from "react";
import { Switch, Route } from "react-router-dom";

//* CUSTOM IMPORTS
import { adminPaths, clientPaths, sponsorPaths } from "./paths";
import { useTranslation } from "../hooks/translation";
import AppRoute from "./Route";
import { getVariableUser } from "~/modules/signed/modules/cms/modules/admin/apis/variables";

//* LAYERS[
import { AuthProvider } from "~/modules/auth/hooks";
import { CLIENT_HEADER_VARIANTS } from "~/modules/signed/modules/client/hooks";

//* PROVIDERS
import { HomeProvider } from "~/modules/signed/modules/client/pages/Home/hooks/home";
import { AdminGamificationProvider } from "~/modules/signed/modules/cms/modules/admin/pages/Gamification/hooks";
import { AdminEmailProvider } from "~/modules/signed/modules/cms/modules/admin/pages/Emails/hooks";
import { DownloadMaterialsProvider } from "~/modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/hooks";
import { ProfileProvider } from "~/modules/signed/modules/client/pages/Profile/hooks";

//* SHARED PAGES
import Page404 from "../pages/Page404";

//* AUTH PAGES
import SignIn from "../../modules/auth/pages/SignIn";
import SignUp from "../../modules/auth/pages/SignUp";
import ForgotPassword from "../../modules/auth/pages/ForgotPassword";
import ResetPassword from "../../modules/auth/pages/ResetPassword";
import SigInQrcode from "../../modules/auth/pages/SignInQrcode";

//* USER PAGES
import CompleteSignUp from "../../modules/signed/modules/client/pages/CompleteSignUp";
import BroadcastRooms from "../../modules/signed/modules/client/pages/Home/pages/BroadcastRooms";
import MarketRooms from "../../modules/signed/modules/client/pages/Home/pages/MarketRooms";
import NetworkRooms from "../../modules/signed/modules/client/pages/Home/pages/NetworkRooms";
import Posters from "../../modules/signed/modules/client/pages/Home/pages/Posters";
import MarketRoom from "../../modules/signed/modules/client/pages/MarketRoom";
import BroadcastRoom from "../../modules/signed/modules/client/pages/BroadcastRoom";
import Questions from "../../modules/signed/modules/client/pages/Questions";
import Profile from "../../modules/signed/modules/client/pages/Profile";
import MyCertificates from "../../modules/signed/modules/client/pages/Home/pages/MyCertificates";
import PosterDetails from "../../modules/signed/modules/client/pages/PosterDetails";
import QuestionPreview from "../../modules/signed/modules/client/pages/Questions/pages/QuestionPreview";
import Schedule from "../../modules/signed/modules/client/pages/Home/pages/Schedule";
import DownloadMaterial from "../../modules/signed/modules/client/pages/Home/pages/DownloadMaterials";
import FolderDetail from "../../modules/signed/modules/client/pages/Home/pages/DownloadMaterials/components/Folders/FolderDetail";
import StickyNotes from "../../modules/signed/modules/client/pages/Home/pages/StickyNotes";
import Ranking from "../../modules/signed/modules/client/pages/Ranking";
import MySchedule from "../../modules/signed/modules/client/pages/MySchedule";

//* ADMIN PAGES
import Dashboard from "../../modules/signed/modules/cms/modules/admin/pages/Dashboard";
import AdminBroadcastRooms from "../../modules/signed/modules/cms/modules/admin/pages/BroadcastRooms";
import CreateBroadcastRoom from "../../modules/signed/modules/cms/modules/admin/pages/BroadcastRooms/pages/CreateBroadcastRoom";
import EditBroadcastRoom from "../../modules/signed/modules/cms/modules/admin/pages/BroadcastRooms/pages/EditBroadcastRoom";
import AdminMarketRooms from "../../modules/signed/modules/cms/modules/admin/pages/MarketRooms";
import CreateMarketRoom from "../../modules/signed/modules/cms/modules/admin/pages/MarketRooms/pages/CreateMarketRoom";
import EditMarketRoom from "../../modules/signed/modules/cms/modules/admin/pages/MarketRooms/pages/EditMarketRoom";
import AdminNetworkRooms from "../../modules/signed/modules/cms/modules/admin/pages/NetworkRooms";
import CreateNetworkRoom from "../../modules/signed/modules/cms/modules/admin/pages/NetworkRooms/pages/CreateNetworkRoom";
import EditNetworkRoom from "../../modules/signed/modules/cms/modules/admin/pages/NetworkRooms/pages/EditNetworkRoom";
import AdminUsers from "../../modules/signed/modules/cms/modules/admin/pages/Users";
import UserDetails from "../../modules/signed/modules/cms/modules/admin/pages/Users/pages/UserDetails";
import EditUser from "../../modules/signed/modules/cms/modules/admin/pages/Users/pages/EditUser";
import CreateUser from "../../modules/signed/modules/cms/modules/admin/pages/Users/pages/CreateUser";
import AdminSettings from "../../modules/signed/modules/cms/modules/admin/pages/Settings";
import RegistrationFields from "../../modules/signed/modules/cms/modules/admin/pages/Settings/pages/RegistrationFields";
import AdminCertificates from "../../modules/signed/modules/cms/modules/admin/pages/Certificates";
import CreateCertificate from "../../modules/signed/modules/cms/modules/admin/pages/Certificates/pages/CreateCertificate";
import EditCertificate from "../../modules/signed/modules/cms/modules/admin/pages/Certificates/pages/EditCertificate";
import MyEvents from "../../modules/signed/modules/cms/modules/admin/pages/MyEvents";
import AdminPosters from "../../modules/signed/modules/cms/modules/admin/pages/Posters";
import CreatePoster from "../../modules/signed/modules/cms/modules/admin/pages/Posters/pages/CreatePoster";
import EditPoster from "../../modules/signed/modules/cms/modules/admin/pages/Posters/pages/EditPoster";
import AdminMarketsRoomAccess from "../../modules/signed/modules/cms/modules/admin/pages/Dashboard/pages/MarketRoomsAccess";
import AdminBroadcastsRoomAccess from "../../modules/signed/modules/cms/modules/admin/pages/Dashboard/pages/BroadcastRoomsAccess";
import EditLanguage from "../../modules/signed/modules/cms/modules/admin/pages/Languages/pages/EditLanguage";
import CreateLanguage from "../../modules/signed/modules/cms/modules/admin/pages/Languages/pages/CreateLanguage";
import Notifications from "../../modules/signed/modules/cms/modules/admin/pages/Notifications";
import NotificationsReceived from "../../modules/signed/modules/cms/modules/admin/pages/Notifications/pages/NotificationsReceived";
import NotificationsOpened from "../../modules/signed/modules/cms/modules/admin/pages/Notifications/pages/NotificationsOpened";
import ImportUsers from "../../modules/signed/modules/cms/modules/admin/pages/Users/pages/ImportUsers";
import DownloadMaterials from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials";
import CreateDownloadMaterial from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/CreateDownloadMaterial";
import CreateMaterialForFolder from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/Folders/CreateMaterial";
import EditMaterialForFolder from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/Folders/EditMaterial";
import EditDownloadMaterial from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/EditDownloadMaterial";
import FolderDetails from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/Folders/FolderDetails";
import MaterialDownloaded from "../../modules/signed/modules/cms/modules/admin/pages/DownloadMaterials/pages/MaterialDownloaded";
import Emails from "../../modules/signed/modules/cms/modules/admin/pages/Emails";
import CreateEmail from "../../modules/signed/modules/cms/modules/admin/pages/Emails/pages/CreateEmail";
import EditEmail from "../../modules/signed/modules/cms/modules/admin/pages/Emails/pages/EditEmail";
import EmailPerformance from "../../modules/signed/modules/cms/modules/admin/pages/Emails/pages/EmailPerformance";
import Gamification from "../../modules/signed/modules/cms/modules/admin/pages/Gamification";
import GamificationUserExtract from "../../modules/signed/modules/cms/modules/admin/pages/Gamification/pages/UserExtract";

//* SPONSORS
import SponsorMeetings from "../../modules/signed/modules/cms/modules/sponsor/pages/Meetings";
import SponsorSchedule from "../../modules/signed/modules/cms/modules/sponsor/pages/Schedule";

function Routes() {
  const { translate } = useTranslation();
  const [option, setOption] = React.useState("sign_up");

  React.useEffect(() => {
    const fetch = async () => {
      await getVariableUser({ key: "optionPage" })
        .then(response => {
          setOption(response.data.value);
        })
        .then(setOption("sign_up"));
    };
    fetch();
  }, []);

  return (
    <Switch>
      {/* PUBLIC ROUTES */}
      <AppRoute
        path="/"
        exact
        component={option === "login" ? SignIn : SignUp}
      />
      <AppRoute path="/login" exact component={SignIn} />
      <AppRoute path="/register" exact component={SignUp} />
      <AppRoute path="/forgot-password" exact component={ForgotPassword} />
      <AppRoute path="/reset" exact component={ResetPassword} />
      <AppRoute path="/login/qrcode" exact component={SigInQrcode} />
      <AppRoute
        path="/questions/:roomId/preview"
        isHibrid
        exact
        disableLayer
        component={QuestionPreview}
      />
      {/* CLIENT ROUTES */}
      <AppRoute
        path="/register/complete"
        headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
        exact
        isPrivate
        layer={AuthProvider}
        component={CompleteSignUp}
      />
      <AppRoute
        path={clientPaths.home.broadcastRooms.index}
        headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
        exact
        isPrivate
        provider={HomeProvider}
        component={BroadcastRooms}
      />
      {process.env.REACT_APP_HAS_MARKET_ROOMS === "true" && (
        <AppRoute
          path={clientPaths.home.marketRooms.index}
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          exact
          isPrivate
          provider={HomeProvider}
          component={MarketRooms}
        />
      )}
      {process.env.REACT_APP_HAS_NETWORK_ROOMS === "true" && (
        <AppRoute
          path={clientPaths.home.networkRooms.index}
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          exact
          isPrivate
          provider={HomeProvider}
          component={NetworkRooms}
        />
      )}
      {process.env.REACT_APP_HAS_POSTERS === "true" && (
        <AppRoute
          path={clientPaths.home.posters.index}
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          exact
          isPrivate
          provider={HomeProvider}
          component={Posters}
        />
      )}
      {process.env.REACT_APP_HAS_CERTIFICATE === "true" && (
        <AppRoute
          path={clientPaths.home.certificates.index}
          exact
          isPrivate
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          provider={HomeProvider}
          providerProps={{ disableBanner: true }}
          component={MyCertificates}
        />
      )}
      {process.env.REACT_APP_HAS_STICKY_NOTES === "true" && (
        <AppRoute
          path={clientPaths.home.stickyNotes.index}
          exact
          isPrivate
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          provider={HomeProvider}
          providerProps={{ disableBanner: true }}
          component={StickyNotes}
        />
      )}
      {process.env.REACT_APP_HAS_GAMIFICATION === "true" && (
        <AppRoute
          path={clientPaths.home.ranking.index}
          exact
          isPrivate
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          component={Ranking}
        />
      )}
      {process.env.REACT_APP_HAS_SCHEDULE === "true" && (
        <AppRoute
          path={clientPaths.home.schedule.index}
          exact
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          provider={HomeProvider}
          providerProps={{ disableBanner: true }}
          isPrivate
          component={Schedule}
        />
      )}
      {process.env.REACT_APP_HAS_DOWNLOAD_MATERIALS === "true" && (
        <AppRoute
          path={clientPaths.home.donwloadMaterials.index}
          exact
          headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
          isPrivate
          provider={HomeProvider}
          component={DownloadMaterial}
          providerProps={{ disableBanner: true }}
        />
      )}

      <AppRoute
        path={clientPaths.mySchedule.index}
        exact
        isPrivate
        title={translate("entities.meetings.myMeetings")}
        component={MySchedule}
        provider={HomeProvider}
        providerProps={{ disableBanner: true }}
      />
      <AppRoute
        path={`${clientPaths.home.donwloadMaterials.index}/:folderId`}
        exact
        headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
        isPrivate
        provider={HomeProvider}
        component={FolderDetail}
        providerProps={{ disableBanner: true }}
      />
      <AppRoute
        path={clientPaths.mySchedule.index}
        title={translate("entities.meetings.myMeetings")}
        headerVariant={CLIENT_HEADER_VARIANTS.DEFAULT}
      />
      <AppRoute
        path={`${clientPaths.marketRooms.index}/:partnerId`}
        isPrivate
        returnPath
        component={MarketRoom}
      />
      <AppRoute
        path={`${clientPaths.broadcastRooms.index}/:broadcastRoomId`}
        exact
        isPrivate
        component={BroadcastRoom}
      />
      <AppRoute
        path={`${clientPaths.broadcastRooms.questions}/:roomId`}
        title={translate("common.questions")}
        exact
        isPrivate
        component={Questions}
      />
      <AppRoute
        path={`${clientPaths.posters.index}/:posterId`}
        exact
        isPrivate
        component={PosterDetails}
      />
      {/* SPONSOR ROUTES */}
      <AppRoute
        isAdmin
        isPrivate
        path={sponsorPaths.mettings.index}
        exact
        component={SponsorMeetings}
      />
      <AppRoute
        isAdmin
        isPrivate
        title={translate("entities.meetings.myMeetings")}
        path={sponsorPaths.schedule.index}
        exact
        component={SponsorSchedule}
      />
      <AppRoute
        path="/profile"
        exact
        isPrivate
        component={Profile}
        provider={ProfileProvider}
      />
      <AppRoute
        path="/profile/:userId"
        exact
        isPrivate
        component={Profile}
        provider={ProfileProvider}
      />
      {/* ADMIN ROUTES */}
      <AppRoute
        isAdmin
        title={translate("common.manageLanguages")}
        isPrivate
        path="/admin/languages/create"
        exact
        component={CreateLanguage}
      />
      <AppRoute
        isAdmin
        title={translate("common.manageLanguages")}
        isPrivate
        path="/admin/languages/edit/:languageId"
        exact
        component={EditLanguage}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        path={adminPaths.downloadMaterials.index}
        exact
        provider={DownloadMaterialsProvider}
        component={DownloadMaterials}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/create"
        exact
        component={CreateDownloadMaterial}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/edit/:materialEditId"
        exact
        component={EditDownloadMaterial}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/folder/:folderId"
        exact
        component={FolderDetails}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/folder/:folderId/create"
        exact
        component={CreateMaterialForFolder}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/folder/:folderId/edit/:materialEditId"
        exact
        component={EditMaterialForFolder}
      />
      <AppRoute
        isAdmin
        title={translate("entities.downloadMaterials.downloadableMaterials")}
        isPrivate
        provider={DownloadMaterialsProvider}
        path="/admin/download-materials/downloaded/:materialId"
        exact
        component={MaterialDownloaded}
      />
      <AppRoute
        isAdmin
        title={translate("common.dashboard")}
        isPrivate
        path="/admin/dashboard/broadcast-rooms-access"
        exact
        component={AdminBroadcastsRoomAccess}
      />
      <AppRoute
        isAdmin
        title={translate("common.dashboard")}
        isPrivate
        path="/admin/dashboard/market-rooms-access"
        exact
        component={AdminMarketsRoomAccess}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.dashboard.index}
        exact
        component={Dashboard}
      />
      <AppRoute
        isAdmin
        isPrivate
        title={translate("entities.gamification.singular")}
        path={adminPaths.gamification.index}
        exact
        provider={AdminGamificationProvider}
        component={Gamification}
      />
      <AppRoute
        isAdmin
        isPrivate
        title={translate("entities.gamification.singular")}
        path={`${adminPaths.gamification.userExtract}/:userId`}
        exact
        provider={AdminGamificationProvider}
        component={GamificationUserExtract}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.emails.index}
        title={translate("entities.emails.plural")}
        exact
        component={Emails}
        provider={AdminEmailProvider}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/emails/create"
        title={translate("entities.emails.plural")}
        exact
        component={CreateEmail}
        provider={AdminEmailProvider}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/emails/edit/:emailId"
        title={translate("entities.emails.plural")}
        exact
        component={EditEmail}
        provider={AdminEmailProvider}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/emails/:emailId"
        title={translate("entities.emails.plural")}
        exact
        component={EmailPerformance}
        provider={AdminEmailProvider}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.notifications.index}
        title={translate("entities.notifications.plural")}
        exact
        component={Notifications}
      />
      <AppRoute
        isAdmin
        title={translate("entities.notifications.plural")}
        isPrivate
        path="/admin/notifications/received/:notificationId"
        exact
        component={NotificationsReceived}
      />
      <AppRoute
        isAdmin
        title={translate("entities.notifications.plural")}
        isPrivate
        path="/admin/notifications/opened/:notificationId"
        exact
        component={NotificationsOpened}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.broadcastRooms.index}
        title={translate("entities.broadcastRooms.plural")}
        exact
        component={AdminBroadcastRooms}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/broadcast-rooms/:categoryId/create"
        title={translate("entities.broadcastRooms.plural")}
        exact
        component={CreateBroadcastRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/broadcast-rooms/edit/:broadcastRoomId"
        title={translate("entities.broadcastRooms.plural")}
        exact
        component={EditBroadcastRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.marketRooms.index}
        title={translate("entities.marketRooms.singular")}
        exact
        component={AdminMarketRooms}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/market-rooms/:marketRoomName/create"
        title={translate("entities.marketRooms.singular")}
        exact
        component={CreateMarketRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/market-rooms/:marketRoomName/edit/:partnerId"
        title={translate("entities.marketRooms.singular")}
        exact
        component={EditMarketRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.networkRooms.index}
        title={translate("entities.networkRooms.plural")}
        exact
        component={AdminNetworkRooms}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/network-rooms/create"
        title={translate("entities.networkRooms.plural")}
        exact
        component={CreateNetworkRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/network-rooms/edit/:networkRoomId"
        title={translate("entities.networkRooms.plural")}
        exact
        component={EditNetworkRoom}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.users.index}
        title={translate("entities.users.plural")}
        exact
        component={AdminUsers}
      />
      <AppRoute
        isAdmin
        isPrivate
        title={translate("entities.users.plural")}
        path={adminPaths.users.import.index}
        component={ImportUsers}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/users/:userId"
        title={translate("entities.users.plural")}
        exact
        component={UserDetails}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/users/edit/:userId"
        title={translate("entities.users.plural")}
        exact
        component={EditUser}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/user/create"
        title={translate("entities.users.plural")}
        exact
        component={CreateUser}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.settings.index}
        title={translate("common.settings")}
        exact
        component={AdminSettings}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/settings/registration-fields"
        title={translate("common.settings")}
        exact
        component={RegistrationFields}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.certificates.index}
        title={translate("entities.certificates.plural")}
        exact
        component={AdminCertificates}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/certificates/create"
        title={translate("entities.certificates.plural")}
        exact
        component={CreateCertificate}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/certificates/:certificateId"
        title={translate("entities.certificates.plural")}
        exact
        component={EditCertificate}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.events.index}
        title={translate("entities.events.mySymplaEvents")}
        exact
        component={MyEvents}
      />
      <AppRoute
        isAdmin
        isPrivate
        path={adminPaths.posters.index}
        title={translate("entities.posters.plural")}
        exact
        component={AdminPosters}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/posters/create"
        title={translate("entities.posters.plural")}
        exact
        component={CreatePoster}
      />
      <AppRoute
        isAdmin
        isPrivate
        path="/admin/posters/:posterId"
        title={translate("entities.posters.plural")}
        exact
        component={EditPoster}
      />
      {/* 404 */}
      <Route component={Page404} />
    </Switch>
  );
}

export default Routes;
