import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { buttonPropTypes, buttonDefaultProps } from "../../atoms";
import { Button, Box } from "./styles";

const buttonGroupPropTypes = {
  /** An Array of <b>Button</b> see <b>Atoms/Button</b> to check all props */
  buttons: PropTypes.arrayOf(PropTypes.shape({ ...buttonPropTypes }))
    .isRequired,

  /** Define all buttons variant, default is "contained" */
  variant: buttonPropTypes.variant,

  /** Define the group orieintation, default is "row" */
  orientation: PropTypes.oneOf(["row", "column"]),

  /** Callback that triggered when click in an button, return the button data */
  onButtonClick: PropTypes.func,

  /** Add an horizontal margin between Buttons, default is 0 */
  marginHorizontal: PropTypes.number,

  /** Add an vertical margin between Buttons, default is 0 */
  marginVertical: PropTypes.number,

  /** Add an animation to Button Group, default is false */
  animated: PropTypes.bool,
};

const buttonGroupDefaultProps = {
  variant: buttonDefaultProps.variant,
  orientation: "row",
  onButtonClick: () => {},
  marginHorizontal: 0,
  marginVertical: 0,
  animated: false,
};

/** An Group of <b>Button</b> component */
function ButtonGroup({
  buttons,
  variant,
  orientation,
  onButtonClick,
  marginHorizontal,
  marginVertical,
  animated,
  ...rest
}) {
  if (!buttons) return null;

  return (
    <Box flexDirection={orientation} animated={animated} {...rest}>
      {buttons.map((button, index) => (
        <Button
          {...button}
          animated={animated}
          isSelected={button.isSelected}
          style={{
            marginLeft: index === 0 ? 0 : marginHorizontal,
            marginTop: index === 0 ? 0 : marginVertical,
          }}
          key={button.id}
          variant={button.variant || variant}
          onClick={() => {
            onButtonClick(button);
            if (button.onClick) button.onClick();
          }}
        >
          {button.children}
        </Button>
      ))}
    </Box>
  );
}

ButtonGroup.propTypes = buttonGroupPropTypes;
ButtonGroup.defaultProps = buttonGroupDefaultProps;

export { ButtonGroup, buttonGroupPropTypes, buttonGroupDefaultProps };
