import React from "react";
import PropTypes from "prop-types";
import { MdSearch, MdClose } from "react-icons/md";

//* CUSTOM IMPORTS
import { Spinner } from "../../atoms";
import { isStringEmpty } from "../../../utils/strings";
import { debounceEvent } from "../../../utils/forms";
import { Container, SearchButton, ClearButton } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const searchBarPropTypes = {
  className: PropTypes.string,
  placeholder: PropTypes.string,

  /** Callback that is triggered when user changes the value of this component.
   *
   * Return the new value.
   *
   * Example: (value) => []
   */
  onChange: PropTypes.func,

  /** If <b>true</b> add an <b>Spinner</b> in this component. */
  isSearching: PropTypes.bool,

  /** If <b>true</b> center the placeholder. */
  centerPlaceholder: PropTypes.bool,

  defaultValue: PropTypes.string,
  /** If you want to add an ID for input */
  inputId: PropTypes.string,
};

const searchBarDefaultProps = {
  className: "search-bar",
  placeholder: null,
  isSearching: false,
  onChange: () => {},
  centerPlaceholder: false,
  defaultValue: null,
  inputId: null,
};

/** The default SearchBar for the hole application */
const SearchBar = ({
  className,
  placeholder,
  onChange,
  isSearching,
  centerPlaceholder,
  defaultValue,
  inputId,
  ...rest
}) => {
  //* REFS
  const inputRef = React.useRef(null);

  //* STATES
  const [isFocused, setIsFocused] = React.useState(false);
  const [searchBar, setSearchBar] = React.useState("");

  //* FUNCTIONS
  const { translate } = useTranslation();

  function onSearchChange() {
    if (inputRef.current) {
      onChange(inputRef.current.value);
      setSearchBar(inputRef.current.value);
    }
  }

  function clearSearchBar() {
    onChange("");
    inputRef.current.value = "";
    setSearchBar("");
  }

  return (
    <Container
      className={className}
      isFocused={isFocused}
      isFilled={!isStringEmpty(searchBar)}
      centerPlaceholder={centerPlaceholder}
      {...rest}
    >
      <SearchButton type="button" title="pesquisar">
        <MdSearch />
      </SearchButton>

      <input
        ref={inputRef}
        type="text"
        defaultValue={defaultValue}
        placeholder={placeholder}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        onChange={debounceEvent(onSearchChange, 500)}
        id={inputId}
      />

      {isSearching && <Spinner size="small" style={{ marginRight: 16 }} />}

      {!isSearching && !isStringEmpty(searchBar) && (
        <ClearButton
          type="button"
          onClick={clearSearchBar}
          title={translate("components.searchBar.clearButton.title")}
        >
          <MdClose />
        </ClearButton>
      )}
    </Container>
  );
};

SearchBar.propTypes = searchBarPropTypes;

SearchBar.defaultProps = searchBarDefaultProps;

export { SearchBar, searchBarPropTypes, searchBarDefaultProps };
