import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  align-items: center;
`;

export const RadioButton = styled.input`
  position: relative;
  cursor: pointer;
  margin-top: 4px;

  &:checked:before {
    content: "";

    width: 15px;
    height: 15px;
    border-radius: 15px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: ${props => props.theme.colors.accent};
    visibility: visible;
  }

  &:checked:after {
    content: "";

    width: 10px;
    height: 10px;
    border-radius: 15px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: ${props => props.theme.colors.accent};
    visibility: visible;
    border: 2px solid #fff;
  }
`;
