import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  likeStyckyNote,
  dislikeStyckyNote,
} from "../../../../../../apis/stickyNotes";

//* CUSTOM IMPORTS
import { getDistanceToNow } from "~/shared/utils/dates";
import { Backdrop } from "~/shared/components/atoms";
import { SwipeTabs } from "~/shared/components/molecules";
import { StickyNoteComments } from "./components/StickyNoteComments";
import { useStickyNotes } from "../../hooks";
import { StickyNoteCell } from "./styles";

const stickyNoteDetailsModalPropTypes = {
  /** This data comes via API */
  data: PropTypes.object.isRequired,
  onLike: PropTypes.func,
  onComment: PropTypes.func,
  onClose: () => {},
};

const stickyNoteDetailsDefaultProps = {
  onLike: () => {},
  onComment: () => {},
  onClose: () => {},
};

const DETAILS_TAB = 0;
const COMMENTS_TAB = 1;

const StickyNoteDetailsModal = ({ data, onClose, ...rest }) => {
  const { handleStickyNoteLike } = useStickyNotes();

  const {
    author,
    created_at: createdAt,
    message,
    color,
    __meta__,
    id,
    is_liked: isLiked,
  } = data;

  //* STATES
  const [tabIndex, setTabIndex] = React.useState(DETAILS_TAB);

  //* FUNCTIONS
  const handleOnLike = async () => {
    handleStickyNoteLike(id);

    if (isLiked) {
      await dislikeStyckyNote({ id });
    } else {
      await likeStyckyNote({ id });
    }
  };

  return (
    <Backdrop {...rest}>
      <SwipeTabs
        disableTabs
        controlled
        onClick={onClose}
        index={tabIndex}
        childrenStyle={{
          height: "100vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
        style={{ width: "100vw" }}
      >
        <StickyNoteCell
          foldPosition="bottomRight"
          variant="post"
          color={color}
          onClick={e => e.stopPropagation()}
          avatar={{ src: author.avatar }}
          title={author.name}
          subTitle={getDistanceToNow(createdAt)}
          content={message}
          onComment={() => setTabIndex(COMMENTS_TAB)}
          onLike={handleOnLike}
          isLiked={isLiked}
          likesCount={__meta__ && __meta__.total_likes}
          commentsCount={__meta__ && __meta__.total_comments}
        />

        <StickyNoteComments
          onClick={e => e.stopPropagation()}
          color={color}
          stickyId={id}
          onBack={() => setTabIndex(DETAILS_TAB)}
        />
      </SwipeTabs>
    </Backdrop>
  );
};

StickyNoteDetailsModal.propTypes = stickyNoteDetailsModalPropTypes;
StickyNoteDetailsModal.defaultProps = stickyNoteDetailsDefaultProps;

export {
  StickyNoteDetailsModal,
  stickyNoteDetailsModalPropTypes,
  stickyNoteDetailsDefaultProps,
};
