import React from "react";

//* APIs
import { sendVeriticationEmail } from "../../modules/admin/apis/emails";

//* CUSTOM IMPORTS
import {
  Input,
  Typography,
  LinkButton,
  Button,
  inputDefaultProps,
  inputPropTypes,
} from "~/shared/components/atoms";
import { LoadingModal } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";

const senderEmailInputPropTypes = {
  variant: inputPropTypes.variant,
};

const senderEmailInputDefaultProps = {
  variant: inputDefaultProps.variant,
};

/** This component extends <b>Input</b> component, to see all props
 * see <b>Atoms/Input</b>.
 */
const SenderEmailInput = ({ variant, ...rest }) => {
  const { translate } = useTranslation();

  //* STATES
  const [showModal, setShowModal] = React.useState(false);
  const [isEmailSent, setIsEmailSent] = React.useState(false);

  //* FUNCTIONS
  const getEmailValue = () => {
    const input = document.getElementById("sender-email");

    return input ? input.value : "";
  };

  const handleSendVerificationEmail = async () => {
    const inputValue = getEmailValue();

    if (inputValue !== "") {
      setShowModal(true);

      await sendVeriticationEmail({
        email: inputValue,
      }).then(() => setIsEmailSent(true));
    }
  };

  const closeModal = () => {
    setShowModal(false);

    setIsEmailSent(false);
  };

  return (
    <>
      <Input
        {...rest}
        variant={variant}
        id="sender-email"
        footerComponent={
          variant === "error" && (
            <Typography className="error" style={{ marginTop: 4 }}>
              {translate("components.senderEmailInput.isInvalid.part1")}

              <LinkButton
                onClick={handleSendVerificationEmail}
                variant="error"
                style={{ margin: "0 4px" }}
              >
                {translate("components.senderEmailInput.isInvalid.part2")}
              </LinkButton>

              {translate("components.senderEmailInput.isInvalid.part3")}
            </Typography>
          )
        }
      />

      <LoadingModal
        style={{ maxWidth: 500 }}
        show={showModal}
        footerComponent={
          isEmailSent && (
            <Button size="small" onClick={closeModal}>
              {translate("common.iUnderstood")}
            </Button>
          )
        }
        onClose={closeModal}
        isLoading={!isEmailSent}
        description={
          isEmailSent ? (
            <>
              {translate(
                "components.senderEmailInput.emailSent.description.part1"
              )}
              <strong style={{ marginLeft: 4 }}>{getEmailValue()}</strong>.{" "}
              {translate(
                "components.senderEmailInput.emailSent.description.part2"
              )}
              .
            </>
          ) : (
            <>
              {translate(
                "components.senderEmailInput.sendingEmail.description"
              )}
              <strong style={{ marginLeft: 4 }}>{getEmailValue()}</strong>...
            </>
          )
        }
        title={
          isEmailSent
            ? translate("components.senderEmailInput.emailSent.title")
            : translate("components.senderEmailInput.sendingEmail.title")
        }
      />
    </>
  );
};

SenderEmailInput.propTypes = senderEmailInputPropTypes;

SenderEmailInput.defaultProps = senderEmailInputDefaultProps;

export { SenderEmailInput };
