import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import Loader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { useUserChat } from "../..";
import { Typography, Spinner, Icon } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import { ChatCell } from "~/shared/components/organisms/cells";
import { Container } from "./styles";

const userListPropTypes = {
  title: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  isPaging: PropTypes.bool.isRequired,
  search: PropTypes.string.isRequired,
};

const UserList = ({ title, onClick, isPaging, search, ...rest }) => {
  const { colors } = React.useContext(ThemeContext);

  return (
    <CellList
      {...rest}
      headerComponent={
        <Typography
          className="accent subTitle bold"
          style={{ marginBottom: 8, padding: "0 8px" }}
        >
          {title}
        </Typography>
      }
      gap={8}
      cellStyle={{ width: "100%" }}
      renderCell={chat => (
        <ChatCell
          avatar={chat.image}
          userName={chat.title}
          isOnline={chat.online}
          onClick={() => onClick(chat)}
          avatarBadgeColor={chat.online ? colors.online : colors.offline}
        />
      )}
      renderCellLoader={() => (
        <Loader avatarSize={56} infosX={74} style={{ padding: "0 16px" }} />
      )}
      emptyComponent={<Empty search={search} variant="search" />}
      footerComponent={isPaging && <Spinner centered />}
    />
  );
};

UserList.propTypes = userListPropTypes;

const ChatList = () => {
  const {
    listChats,
    listChatUsers,
    chats,
    setChats,
    silenceChat,
    isChatListOpen,
    openChat,
  } = useUserChat();
  const { translate } = useTranslation();

  //* STATES
  const [chatUsers, setChatUsers] = React.useState({
    data: [],
    page: 1,
    lastPage: 1,
    isSearching: false,
  });
  const [isPaging, setIsPaging] = React.useState(false);
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      setChats(obj => ({ ...obj, isSearching: true }));

      await listChats(1, search).then(response => {
        setChats({ ...response.data, isLoaded: true });
      });

      setChats(obj => ({ ...obj, isSearching: false }));
    };

    list();
  }, [listChats, search, setChats]);

  React.useEffect(() => {
    const list = async () => {
      setChatUsers(obj => ({ ...obj, isSearching: true }));

      await listChatUsers(1, search).then(response =>
        setChatUsers({ ...response.data })
      );

      setChatUsers(obj => ({ ...obj, isSearching: false }));
    };

    if (search !== "") list();
  }, [search, listChatUsers]);

  const handleOnClick = React.useCallback(
    chat => openChat(chat.title, chat.image, [chat.user_id], chat.user_id),
    [openChat]
  );

  const handleOnSilence = React.useCallback(
    chat => {
      silenceChat(chat.id, !chat.isSilenced);
    },
    [silenceChat]
  );

  const handleOnUserClick = React.useCallback(
    user => openChat(user.title, user.image, [user.id]),
    [openChat]
  );

  const handleUserChatPagination = () => {
    if (isPaging) return;
    if (chatUsers.page < chatUsers.lastPage) {
      setIsPaging(true);

      listChatUsers(chatUsers.page + 1, "").then(response => {
        const { page, lastPage, data } = response.data;

        setChatUsers(obj => ({
          page,
          lastPage,
          data: [...obj.data, ...data],
        }));

        setIsPaging(false);
      });
    }
  };

  const handlePaginate = () => {
    if (isPaging) return;
    if (chats.page < chats.lastPage) {
      setIsPaging(true);

      listChats(chats.page + 1, "").then(response => {
        const { page, lastPage, data } = response.data;

        setChats(obj => ({
          page,
          lastPage,
          data: [...obj.data, ...data],
        }));

        setIsPaging(false);
      });
    }
  };

  const renderData = () => {
    const formmatedData = chats.data.map(chat => {
      const {
        title,
        image,
        __meta__,
        is_silenced: isSilenced,
        last_message_sended_at: lastMessageTime,
        last_message: lastMessage,
        online,
      } = chat;

      return {
        ...chat,
        isSilenced,
        isOnline: online,
        userName: title,
        avatar: image,
        lastMessage,
        messageCounter: __meta__ && __meta__.total_unread_messages,
        lastMessageTime,
      };
    });

    return search ? formmatedData.slice(0, 4) : formmatedData;
  };

  return (
    <Container
      listTitle={search && translate("common.recent")}
      isOpen={isChatListOpen}
      isSearching={chats.isSearching}
      title={translate("entities.chats.title")}
      isLoading={chats.isSearching === true}
      onBottomReached={search ? handleUserChatPagination : handlePaginate}
      loaderListSize={10}
      onSilence={handleOnSilence}
      onSearch={value => setSearch(value)}
      onClick={handleOnClick}
      isPaging={isPaging}
      data={renderData()}
      emptyComponent={
        <Empty
          icon={<Icon iconName="chat" />}
          message={translate("entities.chats.empty.messages")}
          description={translate("entities.chats.empty.description")}
          style={{ marginTop: 40, padding: 10 }}
        />
      }
    >
      {search && (
        <UserList
          containerStyle={{ marginTop: 20 }}
          onClick={handleOnUserClick}
          isLoaded={!chatUsers.isSearching}
          title={translate("common.morePeople")}
          data={chatUsers.data.map(chatUser => ({
            id: chatUser.id,
            title: chatUser.name,
            image: chatUser.avatar,
          }))}
          search={search}
          isPaging={isPaging}
        />
      )}
    </Container>
  );
};

export { ChatList };
