import React from "react";
import PropTypes from "prop-types";
import { MdStar } from "react-icons/md";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { Container, StarList, Star, ContentLoader } from "./styles";

const starsPropTypes = {
  /** Initial number of stars */
  initialRate: PropTypes.number.isRequired,

  /** Callback that is triggered when user click into an star, return the rate of
   * the clicked star
   */
  onClick: PropTypes.func,

  /** Render a title */
  title: PropTypes.string,

  /** Render an Loader instead of <b>Stars</b> */
  isLoaded: PropTypes.bool,

  /** If <b>true</b>, disable the points event for this component */
  readOnly: PropTypes.bool,
};

const starsDefaultProps = {
  onClick: () => {},
  title: null,
  isLoaded: false,
  readOnly: false,
};

const stars = [
  { id: 1, rate: 1 },
  { id: 2, rate: 2 },
  { id: 3, rate: 3 },
  { id: 4, rate: 4 },
  { id: 5, rate: 5 },
];

/** Stars is commonly used for user rating feedback .

 */
function Stars({ initialRate, onClick, title, isLoaded, readOnly, ...rest }) {
  const { colors } = React.useContext(ThemeContext);

  // STATES
  const [auxInitialRate, setAuxInitialRate] = React.useState(initialRate || 0);
  const [rate, setRate] = React.useState(auxInitialRate || 0);

  // When user mose over the start change the rate to the current star hovered
  const handleStarMouseOver = starId => {
    setRate(starId);
  };

  // When user mouse out the star reset the rate
  const handleStartMouseOut = () => {
    setRate(auxInitialRate);
  };

  const handleStartClick = starRate => {
    setAuxInitialRate(starRate);

    onClick(starRate);
  };

  React.useMemo(() => {
    if (isLoaded) {
      setAuxInitialRate(initialRate);
      setRate(initialRate);
    }
  }, [initialRate, isLoaded]);

  return isLoaded ? (
    <Container {...rest}>
      {title && <h4 className="start-title">{title}</h4>}

      <StarList>
        {stars.map((star, index) => (
          <Star
            readOnly={readOnly}
            className="star"
            onClick={() => handleStartClick(star.rate)}
            key={star.id}
            onMouseOver={() => handleStarMouseOver(star.id)}
            onMouseOut={handleStartMouseOut}
            onBlur={handleStartMouseOut}
            onFocus={() => handleStarMouseOver(star.id)}
          >
            <MdStar
              color={
                rate !== null && rate >= index + 1
                  ? colors.accent
                  : colors.smoked
              }
            />
          </Star>
        ))}
      </StarList>
    </Container>
  ) : (
    <ContentLoader id="star-loader" type="stars" size={1} {...rest} />
  );
}

Stars.propTypes = starsPropTypes;

Stars.defaultProps = starsDefaultProps;

export { Stars, starsPropTypes, starsDefaultProps };
