import React from "react";
import { MdLock } from "react-icons/md";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { INPUT_TYPES } from "~/shared/utils/inputs";
import {
  Container,
  AvatarInput,
  DynamicFields,
  AddButton,
  PasswordInput,
  Fieldset,
  Checkbox,
} from "./styles";
import { openRegistrationFieldModal } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";

const textField = {
  type: INPUT_TYPES.TEXT.value,
  deletable: true,
};

function InformationTab({ user, editMode, sendEmail, setSendEmail, ...rest }) {
  const { translate } = useTranslation();

  return (
    <Container>
      <DynamicFields
        noRequiredValidation
        noPassword
        noPasswordValidation
        cellLoaderWidth={500}
        renderHeaderComponent={isLoaded => (
          <AvatarInput
            isLoaded={isLoaded}
            initialValue={user ? user.avatar : null}
            name="avatar"
            labelMessage={
              <>
                <strong>
                  {user ? user.name : translate("common.sendAnNewImage")}
                </strong>
                <h4>
                  {user ? user.email : translate("common.acceptedFormats")}
                </h4>
              </>
            }
          />
        )}
        cellList={{
          orientation: "row",
          cellStyle: {
            width: "calc(50% - 24px)",
            minWidth: "300px",
          },
          lastCell: (
            <>
              <PasswordInput
                type="password"
                icon={<MdLock />}
                name="password"
                title={translate("components.input.password.title")}
                placeholder={
                  editMode
                    ? translate("components.input.password.placeholder")
                    : translate("components.input.password.placeholderEmpty")
                }
              />

              {editMode && (
                <PasswordInput
                  type="password"
                  icon={<MdLock />}
                  name="password_confirmation"
                  title={translate(
                    "components.input.passwordConfirmation.title"
                  )}
                  autocomplete="new-password"
                  placeholder={translate(
                    "components.input.passwordConfirmation.placeholder"
                  )}
                />
              )}

              <div
                style={{
                  minHeight: 72,
                  display: "flex",
                  flexDirection: !editMode ? "column" : "row",
                }}
              >
                <AddButton
                  type="button"
                  placeholder={translate("entities.registrationFields.add")}
                  onClick={() => openRegistrationFieldModal(textField, false)}
                />

                {!editMode && (
                  <Fieldset>
                    <Checkbox
                      checked={sendEmail}
                      onClick={() => setSendEmail(!sendEmail)}
                    />
                    <p>{translate("common.sendEmailWithAccessInstructions")}</p>
                  </Fieldset>
                )}
              </div>
            </>
          ),
        }}
        initialValue={
          user && {
            ...user.attributes,
            ...user,
          }
        }
        {...rest}
        user={user}
      />
    </Container>
  );
}

InformationTab.propTypes = {
  user: PropTypes.object.isRequired,
  editMode: PropTypes.bool,
  sendEmail: PropTypes.bool,
  setSendEmail: PropTypes.func,
};

InformationTab.defaultProps = {
  editMode: false,
  sendEmail: false,
  setSendEmail: () => {},
};

export default InformationTab;
