import React from "react";
import PropTypes from "prop-types";
// CUSTOM IMPORTS
import { SwitchSettingModal } from "~/shared/components/molecules";
import UserAttributes from "../../../pages/Profile/components/ProfileBody/components/GeneralTab/UserAttributeList";

const privacySettingModalPropTypes = {
  user: PropTypes.object.isRequired,
};

const privacySettingModalDefaultPropTypes = {};

function PrivacySettingModal({ user, ...rest }) {
  return (
    <SwitchSettingModal
      {...rest}
      withScroll
      hasFooter
      extraContent={<UserAttributes user={user} hasSettings />}
    />
  );
}

PrivacySettingModal.propTypes = privacySettingModalPropTypes;
PrivacySettingModal.defaultProps = privacySettingModalDefaultPropTypes;

export default PrivacySettingModal;
