import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import { Form, Input, Footer, Checkbox } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function EventForm({ className, onCancel, onSubmit, event, onCheckbox }) {
  // REFS
  const formRef = React.useRef(null);

  // FUNCTIONS
  const { translate } = useTranslation();

  return (
    <Form
      className={className}
      ref={formRef}
      yupValidation={{
        title: Yup.string().required(translate("handlers.field.isRequired")),
        event_id: Yup.string().required(translate("handlers.field.isRequired")),
        user_token: Yup.string().required(
          translate("handlers.field.isRequired")
        ),
        initialDate: Yup.string().required(
          translate("handlers.startDate.isRequired")
        ),
        initialTime: Yup.string().required(
          translate("handlers.startTime.isRequired")
        ),
      }}
      onSubmit={onSubmit}
      initialData={event}
    >
      <Input
        name="title"
        title={translate("components.input.eventTitle.title")}
        placeholder={translate("components.input.eventTitle.placeholder")}
      />

      <div id="event-form-division">
        <Input
          name="event_id"
          title={translate("components.input.eventId.title")}
          placeholder={translate("components.input.eventId.placeholder")}
        />

        <Input
          className="event-form-input"
          name="user_token"
          title={translate("components.input.userToken.title")}
          placeholder={translate("components.input.userToken.placeholder")}
        />
      </div>

      <div id="event-form-division">
        <Input
          title={translate("components.input.startDate.title")}
          name="initialDate"
          type="date"
        />

        <Input
          className="event-form-input"
          name="initialTime"
          type="time"
          title={translate("components.input.startTime.title")}
        />
      </div>

      <Checkbox
        placeholder={translate("common.sendEmailsAutomatically")}
        checked={event ? event.send_email : false}
        onClick={() => {
          onCheckbox(!event.send_email);
        }}
      />

      <Footer onCancel={onCancel} />
    </Form>
  );
}

EventForm.propTypes = {
  className: PropTypes.string,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  event: PropTypes.any,
  onCheckbox: PropTypes.func,
};

EventForm.defaultProps = {
  className: null,
  onCancel: () => {},
  onSubmit: () => {},
  onCheckbox: () => {},
  event: null,
};

export default EventForm;
