import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getAccessLogReportCount } from "~/modules/signed/modules/cms/modules/admin/apis/accessLog";
import { getAccessLogExport } from "~/modules/signed/modules/cms/modules/admin/apis/exports/access";

//* CUSTOM IMPORTS
import { BarChart } from "~/shared/components/organisms";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";
import { DashboardReportCard } from "~/shared/components/molecules";
import { Typography, Trans } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

function AccessCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: accessLogReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getAccessLogReportCount({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardReportCard
      downloadButtonData={{
        callDownloadSheetsApi: () =>
          getAccessLogExport({
            endDate: dateInterval.endDate,
            startDate: dateInterval.startDate,
            timezone,
          }),
        sheetsName: translate("entities.reports.accesses"),
      }}
      style={{ position: "relative" }}
      title={translate("common.accessesAndInteractions")}
      dotColor="#FD9F2B"
      isLoaded={isLoaded}
      orientation="row"
      reportItems={[
        {
          id: 1,
          counter: isLoaded && accessLogReport.unique_access_count,
          description: translate("common.uniqueAccesses"),
        },
        {
          id: 2,
          counter: isLoaded && accessLogReport.access_count,
          description: translate("common.generalAccesses"),
        },
      ]}
    >
      {isLoaded && (
        <BarChart
          chartData={accessLogReport.chartData}
          granularity={accessLogReport.granularity}
          tipDescription={translate("common.interactions")}
        />
      )}

      {isLoaded && (
        <Typography
          className="small bold uppercase opaque"
          style={{
            position: "absolute",
            bottom: 20,
            left: "50%",
            transform: "translateX(-50%)",
          }}
        >
          <Trans translationKey="common.activity">Atividade</Trans>
        </Typography>
      )}
    </DashboardReportCard>
  );
}

AccessCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
  timezone: PropTypes.number.isRequired,
};

export default AccessCard;
