import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  craetePartner,
  updatePartner,
  clearPartner,
} from "~/modules/signed/modules/cms/modules/admin/store/partners/actions";

//* CUSTOM IMPORTS
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { SwipeTabs } from "~/shared/components/molecules";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import { openAdminMarketRooms } from "~/shared/routes/navigation";
import { Footer } from "./styles";

//* TAB FRAGMENTS
import InformationTab from "./components/InformationTab";
import ContactTab from "./components/ContactTab";
import ButtonsTab from "./components/ButtonsTab";
import ExtraContentTab from "./components/ExtraContentTab";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function MarketRoomForm({ partner, marketRoomId }) {
  //* REDUCER
  const dispatch = useDispatch();
  const partnersReducer = useSelector(state => state.adminPartners);
  const { object, list } = partnersReducer && partnersReducer;
  const { tagIds } = object;
  const { isCreating, isUpdating } = list;
  const { data: extraButtons } = useSelector(
    state => state.adminExtraLinks.extraButtons
  );
  const { data: extraEmbeds } = useSelector(
    state => state.adminExtraLinks.extraEmbeds
  );

  //* STATES
  const editMode = isDataValid(partner);
  const [showSaveModal, setShowSaveModal] = React.useState(false);
  const [formData, setFormData] = React.useState({});
  const [formInitialData, setFormInitialData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (editMode) {
      setFormInitialData(partner);
    }

    return () => {
      dispatch(clearPartner());
    };
  }, [dispatch, editMode, partner]);

  // If Craete API status is finished return to list page
  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH")
      openAdminMarketRooms();
  }, [isCreating, isUpdating]);

  function dispatchSave() {
    // Get all Form data input values
    const partnerRoomData = {
      ...formData,
      buttons: { list: [] },
      tagIds,
      extraButtonsIds: extraButtons.map(extraButton => extraButton.id),
      embedIds: extraEmbeds.map(extraEmbed => extraEmbed.id),
    };

    if (editMode) {
      dispatch(updatePartner(partnerRoomData, partner.id));
    } else {
      dispatch(craetePartner(partnerRoomData, marketRoomId));
    }
  }

  // TABS
  const tabs = [
    { id: 1, name: `${translate("common.informations")}`, error: infoTabError },
    { id: 2, name: `${translate("common.contact")}` },
    { id: 3, name: `${translate("common.buttons")}` },
    {
      id: 4,
      name: `${translate("entities.tabs.broadcastTabseExtraContents")}`,
    },
  ];

  return (
    <>
      <Form
        id="partner-form"
        onSubmit={data => {
          setFormData(data);
          setShowSaveModal(true);
        }}
        onError={() => setInfoTabError(true)}
        initialData={formInitialData}
        yupValidation={{
          title: Yup.string().required(
            translate("entities.marketRooms.sponsorRequired")
          ),
          sub_title: Yup.string(),
          bio: Yup.string(),
          email: Yup.string().email("Email inválido."),
          phone: Yup.string(),
          website: Yup.string(),
          logo: Yup.string(),
          walpaper: Yup.string(),
          facebook_url: Yup.string(),
          instagram_url: Yup.string(),
          linkedin_url: Yup.string(),
          website_url: Yup.string(),
          twitter_url: Yup.string(),
          video_url: Yup.string(),
        }}
      >
        <SwipeTabs tabs={tabs} size="small" alignment="left">
          <InformationTab partner={partner} editMode={editMode} />

          <ContactTab />

          <ButtonsTab partner={partner} />

          <ExtraContentTab partner={partner} />
        </SwipeTabs>
      </Form>

      <Footer
        formId="partner-form"
        onCancel={openAdminMarketRooms}
        onConfirm={dispatchSave}
        showModal={showSaveModal}
        modalType={editMode ? "update" : "create"}
        entity={translate("entities.marketRooms.singular")}
        entityName={dTranslate(formData.title)}
        isLoading={isUpdating === "PENDING" || isCreating === "PENDING"}
      />
    </>
  );
}

MarketRoomForm.propTypes = {
  partner: PropTypes.object,
  marketRoomId: PropTypes.number.isRequired,
};

MarketRoomForm.defaultProps = {
  partner: null,
};

export default MarketRoomForm;
