import React from "react";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import { receiveMarketRoomsCategory } from "~/modules/signed/modules/client/store/marketRooms/actions";

//* APIs
import { listMarketRoomCategories } from "~/modules/signed/modules/client/apis/marketRooms";

//* CUSTOM IMPORTS
import { isListValid } from "~/shared/utils/validations";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { CellList, Empty } from "~/shared/components/molecules";
import { Trans, Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { MarketRoomUserViewList } from "../../../../components/lists/MarketRoomUserList";
// import { MarketRoomViewList } from "~/shared/components/organisms/lists";
import { Container, SearchBar } from "./styles";

function MarketRooms() {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: marketRoomsCategories, isLoaded } = useSelector(
    state => state.marketRooms.categories
  );

  //* STATES
  const [isSearching, setIsSearching] = React.useState(false);
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS

  const { translate } = useTranslation();
  React.useEffect(() => {
    const list = async () => {
      await listMarketRoomCategories({ search: "" }).then(response =>
        dispatch(
          receiveMarketRoomsCategory({ marketRoomsCategories: response.data })
        )
      );
    };

    list();
  }, [dispatch]);

  const handleSearch = async s => {
    setIsSearching(true);

    await listMarketRoomCategories({ search: s }).then(response => {
      let auxCategories = response.data;

      if (s !== "") {
        const sortedArray = auxCategories.sort((a, b) => {
          if (isListValid(a.partners) && !isListValid(b.partners)) {
            return -1;
          }
          if (isListValid(b.partners) && !isListValid(a.partners)) {
            return 1;
          }
          return 0;
        });

        auxCategories = sortedArray;
      }

      dispatch(
        receiveMarketRoomsCategory({ marketRoomsCategories: auxCategories })
      );
    });

    setIsSearching(false);
  };

  return (
    <Container>
      <SearchBar
        placeholder={translate("entities.marketRooms.search")}
        onChange={value => {
          setSearch(value);
          handleSearch(value);
        }}
        isSearching={isSearching}
      />

      <CellList
        orientation="column"
        isLoaded={!isSearching && isLoaded}
        data={marketRoomsCategories}
        renderCell={(marketRooms, index) => (
          <MarketRoomUserViewList
            isLoaded={!isSearching && isLoaded}
            search={search}
            isLastOne={index === marketRoomsCategories.length - 1}
            key={marketRooms.id}
            name={marketRooms.name}
            partners={marketRooms.partners}
            level={marketRooms.level}
          />
        )}
        renderCellLoader={() => (
          <div
            style={{
              display: "flex",
              flexWrap: "wrap",
              gap: 20,
              marginBottom: 20,
            }}
          >
            <CellLoader />
            <CellLoader />
            <CellLoader />
          </div>
        )}
        emptyComponent={
          <Empty
            icon={<Icon iconName="marketRooms" />}
            message={
              <Trans translationKey="entities.marketRooms.empty.message">
                Ainda não há nenhum estande cadastrado
              </Trans>
            }
            description={
              <Trans translationKey="entities.marketRooms.empty.description">
                Ainda não há nenhum estande cadastrado
              </Trans>
            }
          />
        }
      />
    </Container>
  );
}

export default MarketRooms;
