import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { EMAIL_TYPES } from "~/shared/utils/types";
import { Typography, Tag, OutlinedButton } from "~/shared/components/atoms";
import {
  LineButton,
  Empty,
  emptyPropTypes,
} from "~/shared/components/molecules";
import { CRUDModal } from "~/shared/components/organisms";
import CardLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import {
  getFullDateBR,
  getFullHours,
  isDateBefore,
} from "~/shared/utils/dates";
import ligthTheme from "~/shared/styles/themes/ligth";
import { CellList, SimpleCell } from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const emailListPropTypes = {
  emails: PropTypes.arrayOf(PropTypes.object).isRequired,
  isLoaded: PropTypes.bool.isRequired,

  /** If <b>true</b> remove the <b>LineButton</b> component at footer. */
  disablePagination: PropTypes.bool,

  /** If <b>true</b> remove the <b>EditButton</b> in fillitens */
  disableEditButton: PropTypes.bool,

  /** If <b>true</b> remove the <b>OutlinedButton</b> component at header. */
  disableAddButton: PropTypes.bool,

  /** Email type control wich type of email will be manipulated.
   * See all EMAIL_TYPES in shared/utils/TypeUtils
   */
  emailType: PropTypes.number.isRequired,

  /** Callback that is triggered when users click to clone an email. Return the clicked email.
   *
   * Example: (email) => {}
   */
  onClone: PropTypes.func,

  /** Callback that is triggered when users click to edit an email.Return the clicked email.
   *
   * Example: (email) => {}
   */
  onEdit: PropTypes.func,

  /** Callback that is triggered when users click to delete an email. Return the clicked email.
   *
   * Example: (email) => {}
   */
  onDelete: PropTypes.func,

  /** Callback that is triggered when users click to add an email */
  onAdd: PropTypes.func,

  /** Callback that is triggered when users click to add an email. Return the clicked email.
   *
   * Example: (email) => {}
   */
  onClick: PropTypes.func,

  /** Callback that is triggered when users click to paginate the list. Return the clicked emailType.
   *
   * Example: (type) => {}
   */
  onPaginate: PropTypes.func,

  title: PropTypes.string.isRequired,

  className: PropTypes.string,

  style: PropTypes.object,

  /** If this props has data, add an <b>Empy</b> component, see all props into
   * <b>Molecules/Empty</b>
   */
  empty: PropTypes.shape(emptyPropTypes),
};

const emailListDefaultProps = {
  disablePagination: false,
  disableAddButton: false,
  disableEditButton: false,
  onClone: () => {},
  onEdit: () => {},
  onDelete: () => {},
  onAdd: () => {},
  onClick: () => {},
  onPaginate: () => {},
  className: null,
  style: null,
  empty: null,
};

const EmailList = ({
  emails,
  isLoaded,
  title,
  emailType,
  disablePagination,
  disableAddButton,
  disableEditButton,
  onClone,
  onAdd,
  onEdit,
  onClick,
  onDelete,
  onPaginate,
  className,
  style,
  empty,
  ...rest
}) => {
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();
  const { colors } = React.useContext(ThemeContext);

  //* STATES
  const [showModal, setShowModal] = React.useState(false);
  const [modalType, setModalType] = React.useState(null);
  const [emailData, setEmailData] = React.useState(null);

  //* FUNCTIONS
  const renderDate = (type, sendedAt, scheduleDate, updatedAt) => {
    if (
      type === EMAIL_TYPES.SCHEDULE &&
      !isDateBefore({
        date: new Date(),
        dateToCompare: scheduleDate,
      })
    )
      return (
        <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
          {translate("entities.emails.card.scheduledAt")}{" "}
          {getFullDateBR(scheduleDate)} - {getFullHours(scheduleDate)}
        </Typography>
      );

    if (type === EMAIL_TYPES.SENDED) {
      if (sendedAt) {
        return (
          <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
            {translate("entities.emails.card.sendedAt")}{" "}
            {getFullDateBR(sendedAt)} - {getFullHours(sendedAt)}
          </Typography>
        );
      }
      return (
        <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
          {translate("entities.emails.card.sendedNow")}
        </Typography>
      );
    }

    if (type === EMAIL_TYPES.SKETCH)
      return (
        <Typography className="small opaque bold" style={{ marginBottom: 8 }}>
          {translate("entities.emails.card.editedAt")}{" "}
          {getFullDateBR(updatedAt)} - {getFullHours(updatedAt)}
        </Typography>
      );
  };

  const renderTag = type => {
    if (type === EMAIL_TYPES.SCHEDULE)
      return (
        <Tag borderRadius={4} color={ligthTheme.colors.accent}>
          {translate("entities.emails.card.scheduled")}
        </Tag>
      );

    if (type === EMAIL_TYPES.SKETCH)
      return (
        <Tag borderRadius={4} color={ligthTheme.colors.gray}>
          {translate("entities.emails.card.sketch")}
        </Tag>
      );

    if (type === EMAIL_TYPES.SENDED)
      return (
        <Tag borderRadius={4} color={ligthTheme.colors.greenCorrect}>
          {translate("entities.emails.card.sended")}
        </Tag>
      );
  };

  const handleOnClone = () => {
    onClone(emailData, emailType);
    setShowModal(false);
  };

  const handleOnDelete = () => {
    onDelete(emailData, emailType);

    setShowModal(false);
  };

  const handleOnClick = email => onClick(email);

  const handleOnModalConfirm = () => {
    if (modalType === "clone") handleOnClone();

    if (modalType === "delete") handleOnDelete();
  };

  const handleOnModalClose = () => {
    setEmailData(null);
    setShowModal(false);
  };

  const fillItems = email => {
    const items = [
      {
        id: 1,
        name: translate("entities.emails.cloneEmail"),
        function: () => {
          setModalType("clone");
          setEmailData(email);
          setShowModal(true);
        },
      },
      {
        id: 2,
        name: translate("entities.emails.editEmail"),
        function: () => onEdit(email),
      },
      {
        id: 3,
        name: translate("entities.emails.deleteEmail"),
        function: () => {
          setModalType("delete");
          setEmailData(email);
          setShowModal(true);
        },
        color: colors.redWrong,
      },
    ];

    if (disableEditButton) items.splice(1, 1);

    return items;
  };

  const renderCell = email => {
    const {
      id,
      name,
      schedule_date: scheduleDate,
      sended_at: sendedAt,
      updated_at: updatedAt,
      type,
    } = email;

    return (
      <SimpleCell
        key={id}
        onClick={() => handleOnClick(email)}
        simpleMenu={{
          fillItems: () => fillItems(email),
        }}
      >
        <div
          style={{
            minHeight: 88,
            minWidth: 300,
            paddingRight: 20,
          }}
        >
          <div style={{ display: "flex", marginBottom: 8 }}>
            {renderTag(type)}
          </div>

          {renderDate(type, sendedAt, scheduleDate, updatedAt)}

          <Typography className="subTitle bold" maxLines={2}>
            {dTranslate(name)}
          </Typography>
        </div>
      </SimpleCell>
    );
  };

  return (
    <div className={className} style={style}>
      <CellList
        {...rest}
        cellId={emailData && emailData.id}
        headerComponent={
          <Typography
            className="bold subTitle accent"
            style={{ marginBottom: 16 }}
          >
            {title}
          </Typography>
        }
        loaderListSize={6}
        data={emails}
        renderCell={email => renderCell(email)}
        isLoaded={isLoaded}
        renderCellLoader={() => (
          <CardLoader
            width={300}
            height={128}
            style={{ height: 128, width: 300 }}
          />
        )}
        gap={20}
        firstCell={
          !disableAddButton && (
            <OutlinedButton
              onClick={onAdd}
              justifyContent="flex-start"
              type="button"
              direction="row"
              placeholder="Criar Email"
              style={{ width: 300, height: 128 }}
            />
          )
        }
        footerComponent={
          !disablePagination && (
            <LineButton
              style={{ marginTop: 30 }}
              onClick={() => onPaginate(emailType)}
            />
          )
        }
        emptyComponent={
          empty && <Empty {...empty} style={{ alignItems: "flex-start" }} />
        }
      />

      <CRUDModal
        show={showModal}
        entity="Email"
        entityName="Email"
        onConfirm={handleOnModalConfirm}
        onClose={handleOnModalClose}
        type={modalType}
      />
    </div>
  );
};

EmailList.propTypes = emailListPropTypes;
EmailList.defaultProps = emailListDefaultProps;

export { EmailList, emailListPropTypes, emailListDefaultProps };
