/* eslint-disable react/prop-types */
import React from "react";

//* CUSTOM IMPORTS
import {
  Touchable,
  ContentWrapper,
  CRUDOptionsMenu,
  Content,
  CustomButtonContent,
} from "./styles";
import { TagGroup } from "../../../../../molecules";
import { Avatar, Typography } from "../../../../../atoms";

/** GoldPartnerCell is an variant of PartnerCell, to see all props see the
 * PartnerCell
 */
const GoldPartnerCell = ({
  title,
  subTitle,
  avatar,
  tags,
  bio,
  crudOptions,
  customButton,
  onClick,
  ...rest
}) => (
  <Touchable {...rest}>
    <ContentWrapper onClick={onClick}>
      <Content className="gold-sponsor-content">
        <Avatar src={avatar} size={56} style={{ marginRight: 8 }} />

        <div className="gold-sponsor-infos">
          <Typography maxLines={1} className="subTitle normal bold">
            {title}
          </Typography>

          {subTitle && (
            <Typography maxLines={1} className="opaque bold small">
              {subTitle}
            </Typography>
          )}

          <TagGroup tags={tags} trim style={{ marginTop: 4 }} />

          {bio && (
            <Typography
              className="opaque small"
              maxLines={4}
              style={{ marginTop: 8 }}
            >
              {bio}
            </Typography>
          )}
        </div>
      </Content>
    </ContentWrapper>

    {crudOptions && (
      <CRUDOptionsMenu
        {...crudOptions}
        className="gold-sponsor-admin-options"
        entityName={title}
      />
    )}

    {customButton && (
      <CustomButtonContent className="gold-sponsor-custom-button">
        {customButton}
      </CustomButtonContent>
    )}
  </Touchable>
);

export { GoldPartnerCell };
