import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";
import TextareaAutosize from "react-autosize-textarea";
import ReactInputMask from "react-input-mask";

//* CUSTOM IMPORTS
import { isStringEmpty } from "../../../utils/strings";
import { InputContainer } from "./components/InputContainer";

const inputPropTypes = {
  /** Name is required to reference the input inside the form  */
  name: PropTypes.string.isRequired,

  /** Add an mask for input, to add the mask Input component use <b>react-input-mask</b> */
  mask: PropTypes.string,

  /** If TRUE change the input for an textArea */
  isTextArea: PropTypes.bool,

  /** Add an title above input */
  title: PropTypes.string,

  className: PropTypes.string,

  /** Add an icon in the left */
  icon: PropTypes.element,

  /** Render an loader instead of the input */
  isLoading: PropTypes.bool,

  /** Add an <b>*</b> in the input title */
  isRequired: PropTypes.bool,

  /** If index exists, will set an zIndex to control overflow layer, when
   * form has many input one above another
   */
  index: PropTypes.number,

  style: PropTypes.object,

  disabled: PropTypes.bool,

  /** Render a component below the Input */
  footerComponent: PropTypes.element,

  variant: PropTypes.oneOf(["error", "default"]),
  /** An warning message to be displayed */
  warning: PropTypes.string,

  /* Tip is information regarding the title  */
  tip: PropTypes.string,

  sizeTip: PropTypes.number,

  tipPosition: PropTypes.oneOf(["right", "top", "botton", "left"]),
};

const inputDefaultProps = {
  isTextArea: false,
  title: null,
  className: null,
  icon: null,
  isLoading: false,
  isRequired: false,
  index: null,
  style: null,
  mask: null,
  warning: null,
  disabled: false,
  footerComponent: null,
  variant: "default",
  tip: null,
  sizeTip: 300,
  tipPosition: "top",
};

/** The Input component use <b>Form</b>  component to anotate the current value, so he
 *  needs to be inside an <b>Form</b>. */
function Input({
  name,
  index,
  mask,
  title,
  className,
  isTextArea,
  icon,
  isRequired,
  isLoading,
  style,
  footerComponent,
  warning,
  disabled,
  variant,
  tip,
  tipPosition,
  sizeTip,
  ...rest
}) {
  const inputRef = React.useRef(null);
  const { fieldName, defaultValue, error, registerField } = useField(name);

  //* STATES
  const [isFocused, setIsFocused] = React.useState(false);
  const [isFilled, setIsFilled] = React.useState(defaultValue || false);

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, inputRef, registerField]);

  const renderInput = () => {
    if (isTextArea)
      return (
        <TextareaAutosize
          defaultValue={defaultValue}
          ref={inputRef}
          {...rest}
        />
      );

    if (mask)
      return (
        <ReactInputMask
          {...rest}
          mask={mask}
          spellCheck={false}
          autoCorrect={false}
          inputRef={ref => {
            inputRef.current = ref;
          }}
          defaultValue={defaultValue}
        />
      );

    return (
      <input
        disabled={disabled}
        spellCheck={false}
        autoCorrect={false}
        ref={inputRef}
        defaultValue={defaultValue}
        {...rest}
      />
    );
  };

  const renderInputContainerVariant = () => {
    if (isFocused) return "focus";

    if (error || variant === "error") return "error";

    if (warning) return "warning";

    if (isFilled) return "filled";

    return null;
  };

  return (
    <InputContainer
      isLoading={isLoading}
      index={index}
      className={className}
      style={style}
      title={title}
      tip={tip}
      isRequired={isRequired}
      variant={renderInputContainerVariant()}
      onFocus={() => setIsFocused(true)}
      onBlur={() => {
        setIsFocused(false);
        setIsFilled(!isStringEmpty(inputRef.current.value));
      }}
      icon={icon}
      errorMessage={error}
      footerComponent={footerComponent}
      warningMessage={warning}
      sizeTip={sizeTip}
      tipPosition={tipPosition}
    >
      {renderInput()}
    </InputContainer>
  );
}

Input.propTypes = inputPropTypes;

Input.defaultProps = inputDefaultProps;

export { Input, InputContainer, inputPropTypes, inputDefaultProps };
