import React from "react";
import PropTypes from "prop-types";
import { MdAddCircle } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listExtraEmbeds,
  deleteExtraEmbed,
  clearExtraEmbeds,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

//* CUSTOM IMPORTS
import { ExtraEmbedCell } from "~/shared/components/organisms/cells/ExtraEmbedCell";
import { IconedButton, Typography, Box } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import { openExtraEmbedModal } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const extraEmbedListPropTypes = {
  title: PropTypes.string.isRequired,

  buttonLabel: PropTypes.string.isRequired,

  /** The room ID the extra button belongs to */
  refId: PropTypes.number.isRequired,

  /** The type of button that will be created, you can acess all the REF_TYPES
   * in <b>modules/signed/modules/admin/store/extraLinks/action</b>.
   */
  refType: PropTypes.string.isRequired,
};

const ExtraEmbedList = ({ title, buttonLabel, refId, refType, ...rest }) => {
  //* REDUCER
  const dispatch = useDispatch();

  const { data: extraEmbeds, isLoaded } = useSelector(
    state => state.adminExtraLinks.extraEmbeds
  );

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && refId) dispatch(listExtraEmbeds(refId, refType));

    return () => {
      dispatch(clearExtraEmbeds());
      isMounted = false;
    };
  }, [dispatch, refId, refType]);

  const onEmbedDelete = embedId => {
    dispatch(deleteExtraEmbed(embedId));
  };

  return (
    <Box {...rest}>
      <Typography
        className="normal small bold uppercase"
        style={{ marginBottom: 10 }}
      >
        {title}
      </Typography>

      <IconedButton
        icon={<MdAddCircle />}
        placeholder={buttonLabel}
        onClick={() => openExtraEmbedModal()}
      />

      <CellList
        data={extraEmbeds}
        renderCell={extraEmbed => (
          <ExtraEmbedCell
            key={extraEmbed.id}
            title={extraEmbed.title && dTranslate(extraEmbed.title)}
            src={extraEmbed.url}
            onDelete={() => onEmbedDelete(extraEmbed.id)}
            onEdit={() => openExtraEmbedModal(extraEmbed)}
          />
        )}
        isLoaded={!refId ? true : isLoaded}
        gap={20}
        containerStyle={{ marginTop: 20 }}
        emptyComponent={
          <Empty
            message={translate("entities.extraEmbeds.empty.message")}
            description={translate("entities.extraEmbeds.empty.description")}
            style={{ alignItems: "flex-start" }}
          />
        }
      />
    </Box>
  );
};

ExtraEmbedList.propTypes = extraEmbedListPropTypes;

export { ExtraEmbedList, extraEmbedListPropTypes };
