import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import { MdKeyboardArrowDown } from "react-icons/md";

//* ACTIONS
import {
  receiveCategories,
  paginateBroadcastRoom,
} from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";

//* APIs
import {
  listBroadcastCategories,
  listBroadcastRooms,
} from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";

//* CUSTOM IMPORTS
import { getFullHoursBetweenDates } from "~/shared/utils/dates";
import { handleSelectItem } from "~/shared/utils/lists";
import { isListValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import { CellList, CheckListItem, Empty } from "~/shared/components/molecules";
import { Typography, Trans, Line } from "~/shared/components/atoms";
import CellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { Scroll, Container, Card, Pagination } from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function CreateBroadcastRoomList({
  selectedBroadcastRooms,
  setSelectedBroadcastRooms,
  isAllSelected,
  onSelect,
  onUnselect,
  onSelectAll,
}) {
  const dispatch = useDispatch();
  const { data: categories, isLoaded } = useSelector(
    state => state.adminBroadcastRooms.categories
  );
  const [isPaging, setIsPaging] = React.useState(false);

  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  //* FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      await listBroadcastCategories({ page: 1 }).then(response => {
        dispatch(receiveCategories({ broadcastRoomCategories: response.data }));
      });
    };

    list();
  }, [dispatch]);

  const onPaginate = async (page, categoryId) => {
    setIsPaging(true);

    await listBroadcastRooms({ page: page + 1, categoryId }).then(response =>
      dispatch(
        paginateBroadcastRoom({ categoryId, broadcastRooms: response.data })
      )
    );

    setIsPaging(false);
  };

  const isCellSelected = cellId => {
    if (isAllSelected) return true;

    if (isListValid(selectedBroadcastRooms)) {
      if (selectedBroadcastRooms.includes(cellId)) return true;
    }

    return false;
  };

  const handleSelect = broadcast =>
    handleSelectItem({
      itemId: broadcast.id,
      list: selectedBroadcastRooms,
      setList: setSelectedBroadcastRooms,
      onSelect: () => onSelect(broadcast),
      onUnselect: () => onUnselect(broadcast),
    });

  const renderCell = category => {
    const { data, page, lastPage } = category.broadcastRooms;
    const hasNextPage = page < lastPage;

    return (
      <CellList
        firstCell={
          <Typography
            className="opaque small bold uppercase"
            style={{ marginLeft: 10, marginTop: 10 }}
          >
            {dTranslate(category.name)}
          </Typography>
        }
        footerComponent={
          hasNextPage && (
            <Pagination>
              {hasNextPage && (
                <button
                  type="button"
                  title="Carregar mais salas"
                  onClick={() => onPaginate(page, category.id)}
                >
                  <Trans translationKey="common.showMore">Mostrar mais</Trans>
                  <MdKeyboardArrowDown />
                </button>
              )}
            </Pagination>
          )
        }
        isPaging={isPaging}
        orientation="column"
        data={data}
        isLoaded={isLoaded}
        renderCell={broadcast => (
          <CheckListItem
            onClick={() => handleSelect(broadcast)}
            title={dTranslate(broadcast.room.title)}
            subTitle={`${getFullHoursBetweenDates(
              broadcast.open_live,
              broadcast.end_live,
              true
            )} ${translate("common.ofContent")}`}
            checkbox={{
              checked: isCellSelected(broadcast.id),
              onClick: () => handleSelect(broadcast),
            }}
          />
        )}
        cellStyle={{ height: 62 }}
        renderCellLoader={() => <CellLoader style={{ marginLeft: 10 }} />}
        emptyComponent={
          <Empty
            size="small"
            message={translate("entities.broadcastRooms.empty.message")}
            description={translate("entities.broadcastRooms.empty.description")}
            style={{ alignItems: "flex-start", paddingLeft: 10 }}
          />
        }
      />
    );
  };

  return (
    <Container>
      <h5>{translate("entities.certificates.scheduleRepresenting")}</h5>

      <Card>
        <CheckListItem
          title={translate("components.input.addAll.title")}
          checkbox={{
            onClick: onSelectAll,
            checked: isAllSelected,
          }}
          onClick={onSelectAll}
        />

        <Line style={{ margin: "0 10px", width: "calc(100% - 20px)" }} />

        <Scroll>
          <CellList
            orientation="column"
            data={categories}
            renderCell={renderCell}
            isLoaded={isLoaded}
          />
        </Scroll>
      </Card>
    </Container>
  );
}

CreateBroadcastRoomList.propTypes = {
  selectedBroadcastRooms: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedBroadcastRooms: PropTypes.func.isRequired,
  isAllSelected: PropTypes.bool.isRequired,
  onSelect: PropTypes.func,
  onUnselect: PropTypes.func,
  onSelectAll: PropTypes.func,
};

CreateBroadcastRoomList.defaultProps = {
  onSelect: () => {},
  onUnselect: () => {},
  onSelectAll: () => {},
};

export default CreateBroadcastRoomList;
