import React from "react";
import { useSelector } from "react-redux";

//* CUSTOM IMPORTS
import AccessCard from "./components/AccessCard";
import MarketRoomsCard from "./components/MarketRoomsCard";
import UsersCard from "./components/UsersCard";
import OnlineCard from "./components/OnlineCard";
import BroadcastRoomsCard from "./components/BroadcastRoomsCard";
import BroadcastRoomAcessCard from "./components/BroadcastRoomAcessCard";
import MarketRoomAccessCard from "./components/MarketRoomAccessCard";

import { getCurrentDay, getTimezone } from "~/shared/utils/dates";
import { DateSelect } from "~/shared/components/organisms";
import { Typography, Trans } from "~/shared/components/atoms";
import { Container, Header, GridContainer } from "./styles";

function Dashboard() {
  const { name } = useSelector(state => state.user.profile.data);
  const timezone = getTimezone({ dateTimeZone: new Date() });

  //* STATES
  const [dateInterval, setDateInterval] = React.useState({
    startDate: getCurrentDay({ inMidnigth: true, subtractDays: 0 }),
    endDate: new Date(),
  });

  return (
    <Container>
      <Typography maxLines={2} className="bigTitle bold centered">
        <Trans translationKey="common.hello">Olá</Trans>, {name}
      </Typography>
      {/* <SearchBar variant="contained" placeholder="Pesquise o que quiser aqui" /> */}

      <Header>
        <Typography className="title bold accent">
          <Trans translationKey="common.dashboard">Dashboard</Trans>
        </Typography>

        <div className="right">
          <DateSelect
            onSelect={newDateInterval => setDateInterval(newDateInterval)}
            initialValue={dateInterval}
          />

          {/* <DownloadButton
            sheetsName="Relatório Geral"

            placeholder="Baixar relatório em .xlsx"
          /> */}
        </div>
      </Header>

      <GridContainer>
        <AccessCard dateInterval={dateInterval} timezone={timezone} />

        <div className="balanced-split-card">
          <MarketRoomsCard dateInterval={dateInterval} timezone={timezone} />
          <MarketRoomAccessCard
            dateInterval={dateInterval}
            timezone={timezone}
          />
        </div>

        <div className="unbalanced-split-card">
          <UsersCard dateInterval={dateInterval} timezone={timezone} />
          <OnlineCard dateInterval={dateInterval} />
        </div>

        <div className="balanced-split-card">
          <BroadcastRoomsCard dateInterval={dateInterval} timezone={timezone} />
          <BroadcastRoomAcessCard
            dateInterval={dateInterval}
            timezone={timezone}
          />
        </div>
      </GridContainer>
    </Container>
  );
}

export default Dashboard;
