import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  listUserChats,
  listUserChatUsers,
  createUserChat,
  getChatMessages,
  getUnreadedMessages,
  updateSettings,
} from "../../apis/chat";

//* SOCKETS
import { useChatSocket } from "../websocket/channels/chat";

//* CUSTOM IMPORTS
import { ChatList } from "./components/ChatList";
import { OpenedChatList } from "./components/OpenedChatList";
import { CHAT_TYPES } from "~/shared/utils/types";
import { useNotification } from "~/modules/signed/hooks/notifications";
import { useSigned } from "~/modules/signed/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { useWindow } from "~/shared/hooks/window";
import { medias } from "~/shared/styles";

const UserChatContext = React.createContext(null);

const INITIAL_DATA = {
  data: [],
  page: 1,
  lastPage: 1,
  isSearching: false,
  isPaging: false,
  isLoaded: false,
};

//* FUNCTIONS
const getExpandedChats = list =>
  list.filter(obj => obj.isMinimized === false).length;

const getLastExpandIndex = list =>
  list.map(obj => obj.isMinimized).lastIndexOf(false);

const UserChatProvider = ({ children }) => {
  //* STATES
  const [unreadMessages, setUnreadMessages] = React.useState(0);
  const [isChatListOpen, setIsChatListOpen] = React.useState(false);
  const [chats, setChats] = React.useState(INITIAL_DATA);
  const [openedsChats, setOpenedChats] = React.useState([]);
  const [chatHistories, setChatHistories] = React.useState([]);

  const { windowWidth } = useWindow();

  const MAX_EXPANDED_CHATS = windowWidth < medias.LARGE_TABLET ? 1 : 1;
  const MAX_OPENED_CHATS = windowWidth < medias.LARGE_TABLET ? 1 : 4;

  //* APIs call
  const listChats = React.useCallback((page, search) => {
    return listUserChats({ page, search });
  }, []);

  const silenceChat = React.useCallback((chatId, isSilenced) => {
    const update = async () => {
      await updateSettings({ chatId, isSilenced }).then(
        ({ data: response }) => {
          const { chat_id, is_silenced } = response;

          setChats(state => {
            const index = state.data.findIndex(chat => chat.id === chat_id);

            if (index !== -1) {
              state.data[index] = {
                ...state.data[index],
                is_silenced,
              };
            }

            return { ...state };
          });
        }
      );
    };

    update();
  }, []);

  const listChatUsers = React.useCallback(
    (page, search) => listUserChatUsers({ page, search }),
    []
  );

  /** Create a new chat with some user or group */
  const createChat = React.useCallback(
    userIds => createUserChat({ type: CHAT_TYPES.INDIVIDUAL, userIds }),
    []
  );

  const getChatHistory = React.useCallback(
    (page, chatId) => getChatMessages({ chatId, page }),
    []
  );

  //* FUNCTIONS
  const { newChatMessage, readMessages, emitReadChatMessage } = useChatSocket();
  const { showNotification, notification } = useNotification();
  const { translate } = useTranslation();
  const { isMyUserId } = useSigned();

  /** When mounting get the unread chats */
  React.useEffect(() => {
    const get = async () => {
      await getUnreadedMessages().then(response =>
        setUnreadMessages(response.data.unread_messages)
      );
    };

    get();
  }, []);

  /** This function will remove all chats openeds and historyChat except the first one */
  React.useMemo(() => {
    if (windowWidth < medias.LARGE_TABLET) {
      setOpenedChats(state => {
        if (state.length > 1) {
          const firstChatOpen = state[0];

          if (firstChatOpen)
            setChatHistories(lastState =>
              lastState.filter(history => history.id === firstChatOpen.id)
            );

          return firstChatOpen ? [firstChatOpen] : state;
        }

        return state;
      });
    }
  }, [windowWidth]);

  const isChatOpen = chatId => openedsChats.some(chat => chat.id === chatId);

  const isChatMinimized = chatId =>
    openedsChats.some(chat => {
      if (chat.id === chatId) {
        if (chat.isMinimized === true) return true;
      }

      return false;
    });

  /** This function will add an message into an specific chatHistory */
  const addChatHistoryMessage = React.useCallback((chatId, messageData) => {
    if (!messageData) return;

    setChatHistories(state => {
      const index = state.findIndex(chatHistory => chatHistory.id === chatId);

      if (index !== -1) {
        state[index].data = [messageData, ...state[index].data];

        return [...state];
      }

      return state;
    });
  }, []);

  /** This function will mark all the messages in newReadMessages as readed inside the
   * chat.
   */
  const markChatMessagesAsReaded = (chatId, newReadMessages) => {
    if (!newReadMessages.length) return;

    if (!chatId) return;

    setChatHistories(state => {
      const chatHistory = state.find(chat => chat.id === chatId);

      if (chatHistory) {
        const messages = chatHistory.data;

        chatHistory.data = messages.map(message => {
          if (newReadMessages.includes(message.id))
            return { ...message, read: true };

          return message;
        });
      }

      return state;
    });
  };

  /** This function deals with all side effect ocurred in chats state and opened state
   * based on socket listener.
   */
  const updateChatListMessage = newMessage => {
    const { created_at, chat_id: chatId, message, chat } = newMessage;

    setOpenedChats(state => {
      const chatIndex = state.findIndex(c => c.id === chatId);

      if (chatIndex !== -1) {
        let currentMessageCounter = state[chatIndex].messageCounter;

        if (isChatMinimized(chatId)) currentMessageCounter += 1;

        state[chatIndex] = {
          ...state[chatIndex],
          messageCounter: currentMessageCounter,
        };
      }

      return state;
    });

    setChats(state => {
      const chatIndex = state.data.findIndex(c => c.id === chatId);

      if (chatIndex !== -1) {
        let currentUnreadMessages = Number(
          state.data[chatIndex].__meta__.total_unread_messages
        );

        /** Increment the unreadMessages only if chat is closed */
        if (!isChatOpen(chatId)) {
          currentUnreadMessages += 1;

          setUnreadMessages(lastState => {
            return lastState + 1;
          });
        }

        const updatedChat = {
          ...state.data[chatIndex],
          last_message: message,
          last_message_sended_at: created_at,
          __meta__: {
            total_unread_messages: currentUnreadMessages,
          },
        };

        /** Deleting the last chat */
        state.data.splice(chatIndex, 1);

        /** Send new chat to the top of the list */
        state.data = [updatedChat, ...state.data];
      } else {
        const unreadMessagesCounter = isChatOpen(chat.id) ? 0 : 1;

        state.data = [
          {
            ...chat,
            last_message: message,
            last_message_sended_at: created_at,
            __meta__: {
              total_unread_messages: unreadMessagesCounter,
            },
          },
          ...state.data,
        ];

        setUnreadMessages(lastState => {
          return lastState + unreadMessagesCounter;
        });
      }

      return state;
    });
  };

  /** This function will set the message counter for as specific chat to 0 */
  const clearChatMessageCounter = chatId => {
    setOpenedChats(state => {
      const chatIndex = state.findIndex(chat => chat.id === chatId);

      if (chatIndex !== -1) {
        state[chatIndex] = { ...state[chatIndex], messageCounter: 0 };
      }

      return state;
    });

    setChats(state => {
      const chatIndex = state.data.findIndex(chat => chat.id === chatId);

      if (chatIndex !== -1) {
        const unredMessagesCounter = Number(
          state.data[chatIndex].__meta__.total_unread_messages
        );

        setUnreadMessages(lastState => lastState - unredMessagesCounter);

        state.data[chatIndex] = {
          ...state.data[chatIndex],
          __meta__: {
            total_unread_messages: 0,
          },
        };
      }

      return state;
    });
  };

  const openChatList = React.useCallback(() => setIsChatListOpen(true), []);

  const closeChatList = React.useCallback(() => setIsChatListOpen(false), []);

  /** This function will close an specific chat. And remove the chat of openedChats, and
   * chatHistories lists.
   */
  const closeChat = React.useCallback(chatId => {
    setOpenedChats(state => state.filter(obj => obj.id !== chatId));
    setChatHistories(state => state.filter(obj => obj.id !== chatId));
  }, []);

  const expandChat = React.useCallback(
    chatId => {
      clearChatMessageCounter(chatId);
      emitReadChatMessage(chatId);

      setOpenedChats(list => {
        if (getExpandedChats(list) >= MAX_EXPANDED_CHATS) {
          const lastExpandedIndex = getLastExpandIndex(list);

          /** Minimize the last expand chat finded */
          list[lastExpandedIndex] = {
            ...list[lastExpandedIndex],
            isMinimized: true,
          };
        }

        const filterList = list.filter(obj => obj.id !== chatId);
        const index = list.findIndex(obj => obj.id === chatId);
        list[index] = { ...list[index], isMinimized: false };

        return [list[index], ...filterList];
      });
    },
    [MAX_EXPANDED_CHATS, emitReadChatMessage]
  );

  const minimizeChat = React.useCallback(chatId => {
    setOpenedChats(list => {
      const filterList = list.filter(obj => obj.id !== chatId);
      const index = list.findIndex(obj => obj.id === chatId);
      list[index] = { ...list[index], isMinimized: true };

      return [...filterList, list[index]];
    });
  }, []);

  const focusChatInput = React.useCallback(chatId => {
    const chatInput = document.getElementById(`chat-input-${chatId}`);

    if (chatInput) chatInput.focus();
  }, []);

  const openChat = React.useCallback(
    (title, image, userIds = [], user_id) => {
      let chatId;
      createChat(userIds).then(response => {
        chatId = response.data.id;
        closeChatList();
        focusChatInput(chatId);
        clearChatMessageCounter(chatId);

        setOpenedChats(list => {
          const hasChat = list.find(chat => chat.id === chatId);

          /** If the opened chat is already in openedChats, just expand it */
          if (hasChat) {
            if (hasChat.isMinimized) expandChat(hasChat.id);

            return list;
          }

          /** If achieve the max number of opened chats, delete the last chat */
          if (list.length >= MAX_OPENED_CHATS)
            if (list[list.length - 1]) closeChat(list[list.length - 1].id);

          /** Minimize the last expand chat finded if max expandade chats is reached */
          if (getExpandedChats(list) >= MAX_EXPANDED_CHATS) {
            const lastExpandedIndex = getLastExpandIndex(list);

            list[lastExpandedIndex] = {
              ...list[lastExpandedIndex],
              isMinimized: true,
            };
          }

          const newChat = {
            id: chatId,
            title,
            image,
            messageCounter: 0,
            isMinimized: false,
            userIds,
            user_id,
          };

          /** If has userIds, indicates that is an new chat, so wee need to create the chat.
           * Else just get the chat history.
           */
          getChatHistory(1, chatId).then(res => {
            setChatHistories(state => [
              ...state,
              { id: chatId, isLoaded: true, ...res.data },
            ]);
          });

          return [newChat, ...list];
        });
      });
    },
    [
      MAX_EXPANDED_CHATS,
      MAX_OPENED_CHATS,
      closeChat,
      closeChatList,
      createChat,
      expandChat,
      focusChatInput,
      getChatHistory,
    ]
  );

  const addChatHistoryMessages = React.useCallback((chatId, response) => {
    setChatHistories(state => {
      const index = state.findIndex(c => c.id === chatId);

      if (index !== -1) {
        state[index] = {
          ...state[index],
          ...response,
          data: [...state[index].data, ...response.data],
        };

        return [...state];
      }

      return state;
    });
  }, []);

  /** Return the history stored inside chatHistory based on chatId */
  const getChatHistoryData = React.useCallback(
    chatId => {
      const index = chatHistories.findIndex(c => c.id === chatId);

      if (index !== -1) return chatHistories[index];

      return { data: [], isLoaded: false, page: 1, lastPage: 1, perPage: 1 };
    },
    [chatHistories]
  );

  /** This function will monitorate every time an new chatMessage socket arrive. And then
   * will show an notification.
   */
  React.useMemo(() => {
    if (newChatMessage) {
      const {
        user,
        created_at,
        chat_id: chatId,
        message,
        chat,
        is_silenced: isSilenced,
      } = newChatMessage;

      updateChatListMessage(newChatMessage);

      /** Only show the notification if the message is not mine. */
      if (!isMyUserId(user.id)) {
        /** Only shows an notification if chat is not open, and there is no current notification */
        if (!isChatOpen(chatId)) {
          const notificationContent = {
            id: chatId,
            created_at,
            title: `${translate("entities.chats.notification.part1")} ${
              user.name
            }`,
            description: message,
            icon: "chat",
            onClick: () =>
              openChat(chat.title, chat.image, [chat.user_id], chat.user_id),
          };

          if (!isChatListOpen && !isSilenced) {
            if (!notification) {
              showNotification(notificationContent);
            } else if (notification.id !== chatId)
              showNotification(notificationContent);
          }
        } else if (!isChatMinimized(chatId))
          /** If chat is open and will receive an message that belongs to this chat, emit
           * that will receive the message
           */
          emitReadChatMessage(chatId);
      }

      addChatHistoryMessage(chatId, newChatMessage);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [newChatMessage]);

  /** This function will monitorate chat socket every time an user read your messages */
  React.useMemo(() => {
    if (readMessages) {
      const { chat_id, new_read_messages } = readMessages;

      markChatMessagesAsReaded(chat_id, new_read_messages);
    }
  }, [readMessages]);

  return (
    <UserChatContext.Provider
      value={{
        //* APIs call
        listChats,
        listChatUsers,
        getChatHistory,
        createChat,
        silenceChat,

        //* States
        chats,
        openedsChats,
        chatHistories,
        isChatListOpen,
        unreadMessages,

        //* Functions
        openChat,
        closeChat,
        expandChat,
        minimizeChat,
        openChatList,
        closeChatList,
        getChatHistoryData,
        focusChatInput,
        setChats,
        addChatHistoryMessages,
      }}
    >
      {children}
      <ChatList />
      <OpenedChatList />
    </UserChatContext.Provider>
  );
};

UserChatProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useUserChat = () => {
  const context = React.useContext(UserChatContext);

  if (!context) throw new Error("useUserChat must be within UserChatProvider");

  return context;
};

export { UserChatProvider, useUserChat };
