import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import history from "~/shared/services/history";
import api, { socketApi } from "~/shared/services/api";
import types from "./types";
import {
  signInSuccess,
  signFailure,
  signUpSuccess,
  signUpFailure,
  recoverPasswordSuccessful,
  recoverPasswordFailed,
  resetPasswordSuccessful,
  resetPasswordFailed,
} from "./actions";
import { validResponseStatus } from "~/shared/utils/apis";

const setDefaultsHeaders = (token, email) => {
  api.defaults.headers.common["X-User-Token"] = token;
  api.defaults.headers.common["X-User-Email"] = `1#${email}`;

  socketApi.defaults.headers.common["X-User-Token"] = token;
  socketApi.defaults.headers.common["X-User-Email"] = `1#${email}`;
};

export function* signIn({ payload }) {
  try {
    const { email, password } = payload;

    const response = yield call(api.post, "/sessions", {
      email,
      password,
    });

    if (validResponseStatus(response.status)) {
      const { user } = response.data;
      const { authentication_token: token } = user;

      yield setDefaultsHeaders(token, email);

      yield put(signInSuccess(token, user, email));
    }
  } catch (err) {
    yield put(signFailure());
  }
}

export function* signInApp({ payload }) {
  try {
    const { email, token } = payload;

    yield setDefaultsHeaders(token, email);

    const response = yield call(api.get, `/users/1`);

    if (validResponseStatus(response.status)) {
      const user = response.data;

      yield put(signInSuccess(token, user, email));
    }
  } catch (err) {
    yield put(signFailure());
  }
}

export function* signInQrcode({ payload }) {
  try {
    const { user, token } = payload;

    // SET API DEFAULT HEADERS
    setDefaultsHeaders(token, user.email);

    yield put(signInSuccess(token, user, user.email));
  } catch (err) {
    yield put(signFailure());
  }
}

export function* signUp({ payload }) {
  try {
    const { name, email, password } = payload;

    const response = yield call(api.post, "/users", {
      name,
      email,
      password,
    });

    // Accept only response with status 200
    if (validResponseStatus(response.status)) {
      const {
        name: userName,
        attributes,
        id,
        authentication_token: token,
      } = response.data;

      const createdUser = {
        id,
        name: userName,
        email,
        attributes,
      };

      yield setDefaultsHeaders(token, email);

      yield put(signUpSuccess(createdUser, token));
    }
  } catch (err) {
    yield put(signUpFailure());
  }
}

export function* recoverPassword({ payload }) {
  try {
    const { email } = payload;

    const response = yield call(api.post, `/forgot`, { email });

    if (validResponseStatus(response.status)) {
      yield put(recoverPasswordSuccessful());
    }
  } catch (err) {
    console.log(err);

    yield put(recoverPasswordFailed());
  }
}

export function* resetPassword({ payload }) {
  try {
    const {
      token,
      newPassword: password,
      confirmPassword: password_confirmation,
    } = payload;

    const response = yield call(api.post, `/reset`, {
      token,
      password,
      password_confirmation,
    });

    if (validResponseStatus(response.status)) {
      const { user } = response.data;
      const { authentication_token: authToken, email } = user;

      setDefaultsHeaders(authToken, email);

      yield put(signInSuccess(authToken, user, email));
      yield put(resetPasswordSuccessful());
    }
  } catch (err) {
    console.log(err);

    yield put(resetPasswordFailed());
  }
}

export function setToken({ payload }) {
  if (!payload) return;

  const { token, email } = payload.auth;

  if (token) {
    setDefaultsHeaders(token, email);
  }
}

export function signOut({ payload }) {
  history.push(payload.returnPath);
}

export default all([
  takeLatest(types.SIGN_IN.REQUEST, signIn),
  takeLatest("@auth/SIGN_IN_QRCODE", signInQrcode),
  takeLatest("@auth/SIGN_IN_APP", signInApp),
  takeLatest(types.SIGN_UP.REQUEST, signUp),
  takeLatest(types.RECOVER_PASSWORD.REQUEST, recoverPassword),
  takeLatest(types.RESET_PASSWORD.REQUEST, resetPassword),
  takeLatest("@auth/SIGN_OUT", signOut),
  takeLatest("persist/REHYDRATE", setToken),
]);
