import React, { createContext } from "react";
import { useSelector } from "react-redux";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import ws from "~/shared/services/webSocket";
import { isDataValid } from "~/shared/utils/validations";

//* CHANNELS
import { AccessLogProvider } from "./channels/accessLog";
import { NotificationSocketProvider } from "./channels/notification";

const SocketContext = createContext(null);

const SocketProvider = ({ children }) => {
  const { token, email } = useSelector(state => state.auth);

  //* REF
  const con = React.useRef(false);

  //* STATES
  const [isConnected, setIsConnected] = React.useState(false);
  con.current = isConnected;

  //* FUNCTIONS
  const connectSocket = React.useCallback(() => {
    if (ws && con.current === false) {
      const auth = {
        xtoken: token,
        xemail: String(email).toLocaleLowerCase(),
      };

      ws.withApiToken(JSON.stringify(auth)).connect();

      ws.on("open", () => {
        setIsConnected(true);
      });

      ws.on("error", error => {
        console.log(error);
      });

      ws.on("close", () => {
        setIsConnected(false);
      });
    }
  }, [email, token]);

  /** Try to connect socket when this component mount, and
   * every time user changes page and when user reconnect with network.
   */
  React.useEffect(() => {
    connectSocket();
  }, [connectSocket]);

  /** Close socket when this component unmount */
  React.useEffect(() => {
    return () => {
      if (ws) {
        if (ws._connectionState === "open") if (ws.ws) ws.close();
      }
    };
  }, [email, token]);

  /** Return if socket is already subscribed into a specific channel */
  const isChannelSubscribed = channel =>
    isDataValid(ws.getSubscription(channel));

  const subscribe = React.useCallback(
    channel => {
      if (isConnected)
        return ws.getSubscription(channel) || ws.subscribe(channel);

      return null;
    },
    [isConnected]
  );

  const closeChannel = React.useCallback(channel => {
    if (channel) if (channel._state === "open") channel.close();
  }, []);

  return (
    <SocketContext.Provider
      id="socket-provider"
      value={{
        isConnected,

        //* Functions
        subscribe,
        closeChannel,
        isChannelSubscribed,
      }}
    >
      <AccessLogProvider>
        <NotificationSocketProvider>{children}</NotificationSocketProvider>
      </AccessLogProvider>
    </SocketContext.Provider>
  );
};

SocketProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useSocket() {
  const context = React.useContext(SocketContext);

  if (!context) throw new Error("useSocket must be used within SocketProvider");

  return context;
}

export { SocketProvider, useSocket };
