import produce from "immer";

import types from "./types";
import { isDataValid } from "~/shared/utils/validations";

const INITIAL_STATE = {
  list: {
    data: [],
    page: 1,
    lastPage: 1,
    isPaging: false,
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },
};

export default function adminNotificationReducer(
  state = INITIAL_STATE,
  action
) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.listNotifications.SUCCESSFUL: {
        const { notifications, page, lastPage } = action.payload;

        if (draft.list.page > 1) {
          draft.list.data = [...draft.list.data, ...notifications];
          draft.list.isPaging = false;
        } else {
          draft.list.data = notifications;
        }

        draft.list.page = page;
        draft.list.lastPage = lastPage;

        draft.list.isLoaded = true;
        break;
      }

      case types.paginateNotifications.REQUEST: {
        draft.list.isPaging = true;
        draft.list.page += 1;

        break;
      }

      // CREATE
      case types.createNotification.REQUEST: {
        draft.list.isCreating = "PENDING";

        break;
      }
      case types.createNotification.SUCCESSFUL: {
        const { notification } = action.payload;

        if (isDataValid(notification)) {
          const notifications = draft.list.data;
          notifications.unshift(notification);

          draft.list.data = notifications;
        }

        draft.list.isCreating = "FINISH";

        break;
      }
      case types.createNotification.FAILED: {
        draft.list.isCreating = "FAILED";

        break;
      }

      // UPDATE
      case types.updateNotification.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.updateNotification.SUCCESSFUL: {
        const { notification } = action.payload;

        if (isDataValid(notification)) {
          const notifications = draft.list.data;
          const notificationIndex = notifications.findIndex(
            s => s.id === notification.id
          );

          notifications[notificationIndex] = notification;

          draft.list.data = notifications;
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.updateNotification.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.deleteNotification.REQUEST: {
        draft.list.isDeleting = "PENDING";

        break;
      }
      case types.deleteNotification.SUCCESSFUL: {
        const { notificationId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== notificationId
        );

        draft.list.data = filteredArray;

        draft.list.isDeleting = "FINISH";

        break;
      }
      case types.deleteNotification.FAILED: {
        draft.list.isDeleting = "FAILED";

        break;
      }

      case types.clear.REQUEST: {
        draft.list.isUpdating = null;
        draft.list.isDeleting = null;
        draft.list.isCreating = null;
        draft.list.isLoaded = false;
        draft.list.page = 1;
        draft.list.lastPage = 1;
        draft.list.isPaging = false;
        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isUpdating = null;
        draft.list.isDeleting = null;
        draft.list.isCreating = null;
        draft.list.isLoaded = false;
        draft.list.page = 1;
        draft.list.lastPage = 1;
        draft.list.isPaging = false;
        break;
      }
      default:
    }
  });
}
