import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Modal, modalPropTypes, modalDefaultProps } from "../../molecules";
import { Typography, Spinner, Icon, Box } from "../../atoms";

const loadingModalPropTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string,

  /** Default variant is "default" */
  variant: PropTypes.oneOf(["default", "error"]),

  /** Render an the errorMessage instead of description  */
  errorMessage: PropTypes.string,

  onClose: modalPropTypes.onClose,

  /** If <b>true</b> render an <b>Spinner</b> at bottom of the Modal, Bu default this balue is <b>true</b>. */
  isLoading: PropTypes.bool,

  style: PropTypes.object,

  /** If this props has data render an element at bottom of Modal */
  footerComponent: PropTypes.elementType,
};

const loadingModalDefaultProps = {
  description: null,
  errorMessage: null,
  variant: "default",
  onClose: modalDefaultProps.onClose,
  isLoading: true,
  style: null,
  footerComponent: null,
};

/** An Modal with an <Spinner/>, commonly used when usre needs to wait a little bit
 * more than usual
 */
function LoadingModal({
  title,
  description,
  errorMessage,
  variant,
  onClose,
  isLoading,
  style,
  footerComponent,
  ...rest
}) {
  const hasError = variant === "error";

  return (
    <Modal
      {...rest}
      disableClose={!hasError}
      onClose={onClose}
      style={{
        padding: 20,
        ...style,
      }}
    >
      <Typography
        className="tinyTitle bold centered normal"
        style={{ marginBottom: 10 }}
      >
        {title}
      </Typography>

      {!hasError && description && (
        <Typography className="centered opaque" style={{ marginBottom: 40 }}>
          {description}
        </Typography>
      )}

      {hasError ? (
        <Box display="flex" alignItems="center" margin="10px auto">
          <Icon variant="error" iconName="error" size={30} />

          <Typography className="bold  error" style={{ marginLeft: 10 }}>
            {errorMessage}
          </Typography>
        </Box>
      ) : (
        isLoading && <Spinner centered size="large" />
      )}

      {footerComponent}
    </Modal>
  );
}

LoadingModal.propTypes = loadingModalPropTypes;

LoadingModal.defaultProps = loadingModalDefaultProps;

export { LoadingModal, loadingModalPropTypes, loadingModalDefaultProps };
