import React from "react";
import { useSigned } from "~/modules/signed/hooks";

//* CUSTOM IMPORTS
import { USER_TYPES } from "~/shared/utils/user";
import { SponsorSideMenu } from "../../../modules/sponsor/components/SponsorSideMenu";
import { AdminSideMenu } from "../../../modules/admin/components/AdminSideMenu";

/** This component has the responsability to deal with whick SideMenu will be
 * rendered based on user access_type
 */
const ProviderSideMenu = () => {
  const { user } = useSigned();

  switch (user.access_type) {
    case USER_TYPES.ADMIN.value:
      return <AdminSideMenu />;

    case USER_TYPES.SPONSOR.value:
      return <SponsorSideMenu />;

    default:
      return <></>;
  }
};

export { ProviderSideMenu };
