import styled from "styled-components";
import PerfectScroll from "react-perfect-scrollbar";

export const Scroll = styled(PerfectScroll)`
  overflow-x: ${props =>
    props.orientation === "column" ? "hidden" : "visible"};
  overflow-y: ${props => (props.orientation === "row" ? "hidden" : "visible")};

  .cell-list-cell {
    width: ${props => (props.orientation === "column" ? "100%" : "unset")};
  }
`;

export const Container = styled.div``;

export const List = styled.div`
  display: flex;

  flex-wrap: ${props => {
    if (props.disableWrap) return "unset";

    if (props.withScroll && !props.wrap) return "noWrap";

    return "wrap";
  }};
  gap: ${props => props.gap}px;
`;

export const Cell = styled.div``;
