import React from "react";
import PropTypes from "prop-types";
import { MdAddCircle } from "react-icons/md";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import ReportItemLoader from "../cellLoaders/SingleBarLoader";
import { Empty } from "..";
import { Trans } from "../../atoms";
import {
  Card,
  CardDot,
  Typography,
  AccessReports,
  AccessReportTitle,
  AccessReportCounter,
  LinkButton,
} from "./styles";

const dashboardAccessReportCardPropTypes = {
  /** Determines the Dot Color */
  dotColor: PropTypes.string.isRequired,

  /** Render an Title in the card */
  title: PropTypes.string.isRequired,

  /** Object that contains report items(id, title, acess_count)  */
  accessReportItems: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.object),
    null,
    undefined,
  ]),

  /** Determines the status of the component  */
  isLoaded: PropTypes.bool,

  /** Empty button click handler */
  onEmptyButtonClick: PropTypes.func,

  /** Render a message if the accessReportItems are empty   */
  emptyMessage: PropTypes.string,

  /** Placeholder for the empty button  */
  emptyButtonPlaceholder: PropTypes.string,

  /** LinkButton click handler */
  onLinkClick: PropTypes.func,
};

const dashboardAccessReportCardDefaultProps = {
  accessReportItems: [],
  isLoaded: false,
  onEmptyButtonClick: () => {},
  onLinkClick: () => {},
  emptyMessage: null,
  emptyButtonPlaceholder: null,
};

const DashboardAccessReportCard = ({
  dotColor,
  title,
  accessReportItems,
  isLoaded,
  emptyButtonPlaceholder,
  onEmptyButtonClick,
  onLinkClick,
  emptyMessage,
}) => {
  const { colors } = React.useContext(ThemeContext);

  return (
    <Card>
      <CardDot color={dotColor} />
      <Typography
        maxLines={2}
        className="small bold uppercase opaque"
        style={{ marginBottom: 20, marginLeft: 30 }}
      >
        {title}
      </Typography>

      {!isLoaded ? (
        <div style={{ padding: "0 20px" }}>
          <ReportItemLoader
            style={{ marginTop: 10, marginBottom: 30, width: "90%" }}
            height={10}
          />
          <ReportItemLoader
            style={{ marginBottom: 30, width: "90%" }}
            height={10}
          />
          <ReportItemLoader
            style={{ marginBottom: 30, width: "90%" }}
            height={10}
          />
        </div>
      ) : (
        <>
          {accessReportItems.length === 0 ? (
            <Empty
              message={emptyMessage}
              button={{
                children: emptyButtonPlaceholder,
                icon: <MdAddCircle />,
                onClick: onEmptyButtonClick,
              }}
              size="small"
            />
          ) : (
            <AccessReports>
              {accessReportItems.slice(0, 3).map(accessReportItem => (
                <li key={accessReportItem.id}>
                  <AccessReportTitle maxLines={1}>
                    {accessReportItem.title}
                  </AccessReportTitle>
                  <AccessReportCounter maxLines={1}>
                    {accessReportItem.access_count}
                  </AccessReportCounter>
                </li>
              ))}

              <LinkButton
                fontColor={colors.accent}
                variant="text"
                onClick={onLinkClick}
              >
                <Trans translationKey="common.seeItAll">Ver Tudo</Trans>
              </LinkButton>
            </AccessReports>
          )}
        </>
      )}
    </Card>
  );
};

DashboardAccessReportCard.propTypes = dashboardAccessReportCardPropTypes;

DashboardAccessReportCard.defaultProps = dashboardAccessReportCardDefaultProps;

export { DashboardAccessReportCard };
