import React from "react";
import { Form } from "@unform/web";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import {
  userTypesOptions,
  convertUserType,
  USER_TYPES,
} from "~/shared/utils/user";
import { Container, Groups, Dropdown } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import { PartnerCheckList } from "~/modules/signed/modules/cms/components/lists";

function GroupsTab({
  selectedGroups,
  setSelectedGroups,
  accessType,
  setAccessType,
  selectedPartners,
  setSelectedPartners,
}) {
  const { translate } = useTranslation();

  return (
    <Container>
      <Groups
        title={translate("entities.users.groupsThatThisUserIsPart")}
        selectedGroups={selectedGroups}
        setSelectedGroups={setSelectedGroups}
      />

      <Form style={{ flex: 1, maxWidth: 350 }}>
        <Dropdown
          name="userType"
          initialValue={convertUserType({ userType: accessType })}
          title={translate("entities.users.type")}
          options={userTypesOptions}
          onChange={value => setAccessType(value)}
        />

        {accessType === USER_TYPES.SPONSOR.value && (
          <PartnerCheckList
            selectedPartners={selectedPartners}
            setSelectedPartners={setSelectedPartners}
            title={translate("common.linkSponsor")}
            style={{ marginTop: 16 }}
          />
        )}
      </Form>
    </Container>
  );
}

GroupsTab.propTypes = {
  selectedGroups: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedGroups: PropTypes.func.isRequired,
  accessType: PropTypes.string,
  setAccessType: PropTypes.func,
};

GroupsTab.defaultProps = {
  accessType: null,
  setAccessType: () => {},
};

export default GroupsTab;
