import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

//* CUSTOM
import { RichTextEditor } from "~/shared/components/organisms";

const adminTranslateTextEditorPropTypes = {
  name: PropTypes.string.isRequired,

  /** Callback that is triggered when user change the language, returns the new language */
  onLanguageChange: PropTypes.func,

  /** Callback that is triggered when users change the value of the <b>RichTextEditor</b>,
   * return the jsonTranslation
   */
  onChange: PropTypes.func,
};

const adminTranslateTextEditorDefaultProps = {
  onLanguageChange: () => {},
  onChange: () => {},
};

/** This component use <b>RichTextEditor</b> component, to see all props consult
 * <b>organisms/RichTextEditor</b>
 */
const AdminTranslateTextEditor = ({
  name,
  onChange,
  onLanguageChange,
  ...rest
}) => {
  const {
    currentLanguage: globalLanguage,
    languages,
    createJsonTranslation,
    getTranslateByRefCode,
    updateTranslateByRefCode,
  } = useDynamicTranslation();

  const { defaultValue } = useField(name);
  const { fieldName, registerField } = useField(`${name}-translation`);

  //* REF
  const inputRef = React.useRef(null);
  const langRef = React.useRef(globalLanguage);

  //* CUSTOM
  const [isTheFirstChange, setIsTheFirstChange] = React.useState(true);
  const [language, setLanguage] = React.useState(globalLanguage);
  const [textValue, setTextValue] = React.useState("");
  const [values, setValues] = React.useState({});

  //* FUNCTIONS
  langRef.current = language;

  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, inputRef, registerField]);

  React.useEffect(() => setValues(createJsonTranslation(defaultValue)), [
    createJsonTranslation,
    defaultValue,
  ]);

  /** Change language every time the globalLang changes */
  React.useMemo(() => setLanguage(globalLanguage), [globalLanguage]);

  React.useMemo(() => onLanguageChange(language), [language, onLanguageChange]);

  /** Change inputValue every time inputLang changes */
  React.useMemo(() => {
    if (language && values)
      setTextValue(getTranslateByRefCode(values, langRef.current.refCode));
  }, [getTranslateByRefCode, language, values]);

  /** Add the value of the current language into the JSON values */
  const handleOnChange = value => {
    setTextValue(value);

    if (isTheFirstChange) {
      setIsTheFirstChange(false);
    } else {
      const jsonValue = updateTranslateByRefCode(
        value,
        values,
        langRef.current.refCode
      );

      onChange(jsonValue);

      setValues(() => jsonValue);
    }
  };

  return (
    <>
      <RichTextEditor
        {...rest}
        onChange={handleOnChange}
        value={textValue}
        languageMenu={{
          buttonCountryCode: language && language.countryCode,
          data: languages.map(lang => ({
            id: lang.ref_code,
            name: lang.title,
            countryCode: lang.flag_icon,
          })),
          onFlagClick: flag => setLanguage({ ...flag, refCode: flag.id }),
        }}
      />

      <input
        ref={inputRef}
        style={{ display: "none" }}
        value={JSON.stringify(values)}
      />
    </>
  );
};

AdminTranslateTextEditor.propTypes = adminTranslateTextEditorPropTypes;

AdminTranslateTextEditor.defaultProps = adminTranslateTextEditorDefaultProps;

export { AdminTranslateTextEditor, adminTranslateTextEditorPropTypes };
