import React from "react";
import PropTypes from "prop-types";

import UpdateProfileFragment from "../../fragments/UpdateProfileFragment";

import { Container } from "./styles";

const profileEditModalPropTypes = {
  show: PropTypes.bool.isRequired,
};

const profileEditModalDefaultPropTypes = {};

function ProfileEditModal({ show, ...rest }) {
  return (
    <Container show={show} withScroll {...rest}>
      <UpdateProfileFragment
        button={{ children: "Salvar perfil" }}
        isCompleteSignUp={false}
      />
    </Container>
  );
}

ProfileEditModal.propTypes = profileEditModalPropTypes;
ProfileEditModal.defaultProps = profileEditModalDefaultPropTypes;

export { ProfileEditModal, profileEditModalPropTypes };
