import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { ProfileConnectionCell } from "./styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { CONNECTION_TYPES } from "~/shared/utils/types";
import CheckListCellLoader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import DisconnectModal from "~/modules/signed/modules/client/components/modals/DisconnectModal";
import { SearchBar } from "../../styles";
import { API_STATUS } from "~/shared/utils/apis";

function AllConnectionsTab({ itsMe, callOther, callConnected }) {
  // STATES
  const {
    connectedConnections,
    otherUserConnections,
    updateConnection,
    paginateConnections,
    profileNavigation,
    isUserLoaded,
    connectedCurrentPage,
    otherCurrentPage,
    isSearching,
    listUserConnections,
    setOtherUserConnections,
    searchString,
    setSearchString,
    setConnectedConnections,
    listConnections,
  } = useProfile();
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();
  const [showDisconnectModal, setShowDisconnectModal] = React.useState(false);
  const [clickedUser, setClickedUser] = React.useState({});
  const [connectionApiStatus, setConnectionApiStatus] = React.useState(
    API_STATUS.IDLE
  );

  React.useEffect(() => {
    if (!connectedConnections.isLoaded && callConnected)
      listConnections(
        connectedConnections.page,
        CONNECTION_TYPES.CONNECTED,
        setConnectedConnections
      );
  }, [
    listConnections,
    connectedConnections.page,
    searchString,
    connectedConnections.isLoaded,
    callConnected,
    setConnectedConnections,
  ]);

  // Fill other user connected connections array
  React.useEffect(() => {
    if (!otherUserConnections.isLoaded && callOther)
      listUserConnections(
        otherUserConnections.page,
        CONNECTION_TYPES.ALL,
        setOtherUserConnections
      );
  }, [
    callOther,
    listUserConnections,
    otherUserConnections.isLoaded,
    otherUserConnections.page,
    setOtherUserConnections,
  ]);

  const fillItems = connected => {
    const items = [
      {
        id: 1,
        name: translate("entities.profile.header.disconnect"),
        function: () => {
          setClickedUser(connected);
          setShowDisconnectModal(true);
        },
        color: colors.redWrong,
      },
    ];

    return items;
  };

  // FUNCTIONS
  function renderMenu(connected) {
    if (itsMe)
      return {
        buttonIcon: <Icon iconName="menu" />,
        fillItems: () => fillItems(connected),
      };
    return null;
  }

  const renderCell = connected => (
    <ProfileConnectionCell
      infos={{ ...connected, title: connected.name }}
      avatar={{ src: connected.avatar, size: 68 }}
      isDeleting={connected.isDeleting}
      simpleMenu={renderMenu(connected)}
      onClick={() => profileNavigation(connected)}
    />
  );

  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={
            itsMe
              ? translate("entities.profile.body.connection.myEmptyAllMessage")
              : translate("entities.profile.body.connection.emptyAllMessage")
          }
          description={
            itsMe
              ? translate(
                  "entities.profile.body.connection.myEmptyAllDescription"
                )
              : translate(
                  "entities.profile.body.connection.emptyAllDescription"
                )
          }
          icon={<Icon iconName="userAdd" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="friends" />}
      />
    );
  };

  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);
          if (itsMe) {
            listConnections(
              connectedConnections.page,
              CONNECTION_TYPES.CONNECTED,
              setConnectedConnections,
              value
            );
          } else {
            listUserConnections(
              otherUserConnections.page,
              CONNECTION_TYPES.ALL,
              setOtherUserConnections,
              value
            );
          }
        }}
      />

      {itsMe ? (
        <>
          <CellList
            isLoaded={
              isUserLoaded &&
              !connectedConnections.isPaging &&
              !isSearching &&
              connectedConnections.isLoaded
            }
            emptyComponent={handleEmptyMessage()}
            renderCellLoader={() => (
              <CheckListCellLoader
                avatarSize={68}
                height={68}
                infosX={90}
                style={{ marginRight: "160px", marginBottom: "32px" }}
              />
            )}
            data={connectedConnections.data}
            renderCell={renderCell}
            loaderListSize={9}
            gap={16}
          />
        </>
      ) : (
        <>
          <CellList
            isLoaded={
              isUserLoaded &&
              !otherUserConnections.isPaging &&
              !isSearching &&
              otherUserConnections.isLoaded
            }
            emptyComponent={handleEmptyMessage()}
            renderCellLoader={() => (
              <CheckListCellLoader
                avatarSize={68}
                height={68}
                infosX={90}
                style={{ marginRight: "160px", marginBottom: "32px" }}
              />
            )}
            data={otherUserConnections.data}
            renderCell={renderCell}
            loaderListSize={9}
            gap={16}
          />
        </>
      )}

      <Pagination
        currentPage={itsMe ? connectedCurrentPage : otherCurrentPage}
        handlePagination={page =>
          paginateConnections(CONNECTION_TYPES.CONNECTED, page)
        }
        lastPage={
          itsMe ? connectedConnections.lastPage : otherUserConnections.lastPage
        }
      />

      <DisconnectModal
        user={clickedUser}
        options={null}
        show={showDisconnectModal}
        onCancel={() => setShowDisconnectModal(false)}
        onClose={() => setShowDisconnectModal(false)}
        isLoading={connectionApiStatus === API_STATUS.DELETING}
        onConfirm={async () => {
          setConnectionApiStatus(API_STATUS.DELETING);

          await updateConnection(
            clickedUser.id,
            CONNECTION_TYPES.DISCONNECTED,
            CONNECTION_TYPES.CONNECTED
          );

          setShowDisconnectModal(false);
          setConnectionApiStatus(API_STATUS.SUCCESS);
        }}
      />
    </div>
  );
}

AllConnectionsTab.propTypes = {
  itsMe: PropTypes.bool.isRequired,
  callOther: PropTypes.bool.isRequired,
  callConnected: PropTypes.bool.isRequired,
};

AllConnectionsTab.defaultProps = {};

export default AllConnectionsTab;
