import types from "./types";

// LIST
export function listTags(tagType) {
  return {
    type: types.LIST.REQUEST,
    payload: { tagType },
  };
}
export function listTagsSuccessful(tags) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { tags },
  };
}

// CREATE
export function createTag(data) {
  return {
    type: types.CREATE.REQUEST,
    payload: { data },
  };
}
export function createTagSuccessful(tag) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { tag },
  };
}

export function createTagFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateTag(tagId, data) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { tagId, data },
  };
}
export function updateTagSuccessful(tag) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { tag },
  };
}
export function updateTagFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteTag(tagId) {
  return {
    type: types.DELETE.REQUEST,
    payload: { tagId },
  };
}
export function deleteTagSuccessful(tagId) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { tagId },
  };
}
export function deleteTagFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// CLEAR
export function clearTags() {
  return {
    type: types.CLEAR.CLEAR_TAGS,
  };
}
