import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, Children } from "./styles";

const homeTemplatePropTypes = {
  bannerElement: PropTypes.element,
  children: PropTypes.element.isRequired,

  /** if <b.true</b> do not apply media query in css */
  disableMedias: PropTypes.bool,
};

const homeTemplateDefaultProps = {
  bannerElement: null,
  disableMedias: false,
};

/** The HomeTemplate is used in the Home pages to control padding and media queries */
const HomeTemplate = ({ children, bannerElement, disableMedias }) => (
  <Container>
    {bannerElement}

    <Children disableMedias={disableMedias}>{children}</Children>
  </Container>
);

HomeTemplate.propTypes = homeTemplatePropTypes;
HomeTemplate.defaultProps = homeTemplateDefaultProps;

export { HomeTemplate, homeTemplatePropTypes, homeTemplateDefaultProps };
