import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import EmailDetails from "../../components/EmailDetails";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminEmails } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminEmail } from "../../hooks";
import { Spinner } from "~/shared/components/atoms";

function EmailPerformance() {
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs } = useCMS();
  const {
    email,
    log,
    showAdminEmail,
    isEmailLoaded,
    clearAdminEmail,
    adminEmailLog,
    isLogLoaded,
  } = useAdminEmail();

  // PARAMS
  const { emailId } = useParams();

  // FUNCTIONS
  React.useEffect(() => {
    if (emailId) showAdminEmail(emailId);

    return () => {
      clearAdminEmail();
    };
  }, [clearAdminEmail, emailId, showAdminEmail]);

  React.useEffect(() => {
    if (emailId) adminEmailLog(emailId);

    return () => {
      clearAdminEmail();
    };
  }, [clearAdminEmail, emailId, adminEmailLog]);

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.emails.plural"),
        function: openAdminEmails,
      },
      {
        id: 2,
        name: translate("entities.emails.emailDetails.report"),
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, translate]);

  return isEmailLoaded && isLogLoaded ? (
    <EmailDetails email={email} log={log} />
  ) : (
    <Spinner centered />
  );
}

export default EmailPerformance;
