import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Icon, iconPropTypes } from "../Icon";
import { Container, Tip } from "./styles";

const tooltipPropTypes = {
  /** Define the Tooltip message */
  message: PropTypes.oneOfType([PropTypes.string, PropTypes.element])
    .isRequired,

  /** Define the Tip color of the Tooltip */
  tipColor: PropTypes.string,

  /** Define the Tooltip variant styles */
  variant: PropTypes.oneOf(["default", "contained"]),

  icon: PropTypes.shape(iconPropTypes).isRequired,
};

const tooltipDefaultProps = {
  tipColor: null,
  variant: "default",
};

/** Tooltips display informative text when users tap on an element. */
function Tooltip({ variant, message, tipColor, icon, ...rest }) {
  // STATES
  const [visibility, setVisibility] = React.useState(false);

  return (
    <Container
      {...rest}
      onMouseEnter={() => setVisibility(true)}
      onMouseLeave={() => setVisibility(false)}
      onClick={() => setVisibility(!visibility)}
    >
      <Icon {...icon} />

      <Tip
        className="scrollable"
        variant={variant}
        color={tipColor}
        visibility={visibility}
      >
        {message}
      </Tip>
    </Container>
  );
}

Tooltip.propTypes = tooltipPropTypes;
Tooltip.defaultProps = tooltipDefaultProps;

export { Tooltip, tooltipPropTypes, tooltipDefaultProps };
