import styled, { keyframes } from "styled-components";

//* CUSTOM IMPORTS
import { layers } from "~/shared/styles";
import { IconedCard } from "~/shared/components/molecules";

const topIn = keyframes`
  from {
    top: -100px;
    opacity: 0;
  }

  to {
    top: 88px;
    opacity: 1;
  }
`;

const topOut = keyframes`
  from {
    top: 88px;
    opacity: 1;
  }

  to {
    top: -100px;
    opacity: 0;
    display: none;
  }
`;

export const GamificationCard = styled(IconedCard)`
  max-width: 60%;
  animation: ${props => (!props.animate ? topIn : topOut)} ease 0.5s;
  position: fixed;
  z-index: ${layers.notification};
  left: 50%;
  top: 88px;
  transform: translateX(-50%);
`;
