import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Tags, Tag, Title } from "./styles";

function FieldsTags({ fieldTags, title, style, onClick }) {
  return (
    <Container style={style}>
      <Title>{title}</Title>
      <Tags>
        {fieldTags.map(tag => (
          <Tag key={tag.id} onClick={() => onClick(tag)}>
            {tag.title}
          </Tag>
        ))}
      </Tags>
    </Container>
  );
}

FieldsTags.propTypes = {
  fieldTags: PropTypes.arrayOf(PropTypes.object).isRequired,
  title: PropTypes.string.isRequired,
  style: PropTypes.object,
  onClick: PropTypes.func.isRequired,
};

FieldsTags.defaultProps = {
  style: null,
};

export default FieldsTags;
