import React from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import * as Yup from "yup";

//* ACTIONS
import {
  createTag,
  deleteTag,
  updateTag,
} from "~/modules/signed/modules/cms/modules/admin/store/tags/actions";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import history from "~/shared/services/history";
import { Box } from "~/shared/components/atoms";
import { ColorInput } from "~/shared/components/molecules";
import { AdminFormModal as FormModal } from "~/modules/signed/modules/cms/components/AdminFormModal";
import { AdminTranslateInput as TranslateInput } from "../../AdminTranslateInput";

const tagModalPropTypes = {
  /** The type for this TAG, you can access all types into <b>shared/utils/TypeUtils</b>
   * in TAG_TYPES constant
   */
  tagType: PropTypes.string.isRequired,

  /** The Tag object to fill the form */
  tag: PropTypes.shape({
    id: PropTypes.number,
    color: PropTypes.string,
    name: PropTypes.any,
  }),
};

const tagModalDefaultProps = {
  tag: null,
};

function TagModal({ tagType, tag }) {
  const dispatch = useDispatch();
  const { isCreating, isUpdating, isDeleting } = useSelector(
    state => state.adminTags.list
  );

  const isEditing = tag;
  const tagId = tag && tag.id;
  const { translate } = useTranslation();

  //* STATES
  const [show, setShow] = React.useState(true);
  const [initialData, setInitialData] = React.useState({});

  //* FUNCTIONS
  React.useEffect(() => {
    if (isEditing) setInitialData(tag);
  }, [isEditing, tag]);

  const handleOnClose = () => {
    setShow(false);

    setTimeout(() => history.goBack(), 300);
  };

  const handleSubmit = data => {
    const { name, color } = data;

    const tagData = {
      name,
      color,
      type: tagType || "",
    };

    if (isEditing) {
      dispatch(updateTag(tagId, tagData));
    } else {
      dispatch(createTag(tagData));
    }
  };

  const handleOnDelete = () => dispatch(deleteTag(tag.id));

  return (
    <FormModal
      enableDeleteButton={isEditing}
      title={
        isEditing
          ? `${translate("entities.marketRooms.editTag")}`
          : `${translate("entities.marketRooms.addTag")}`
      }
      show={show}
      onClose={handleOnClose}
      yupValidation={{
        name: Yup.string().required(
          translate("entities.marketRooms.tagRequired")
        ),
      }}
      onSubmit={handleSubmit}
      onDelete={handleOnDelete}
      isDeleting={isDeleting === "PENDING"}
      initialData={initialData}
      isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
    >
      <Box display="flex" width="100%" alignItems="end">
        <TranslateInput
          name="name"
          title={translate("entities.marketRooms.inputNameTag.title")}
          placeholder={translate(
            "entities.marketRooms.inputNameTag.placeholder"
          )}
          style={{ zIndex: 2 }}
        />

        <ColorInput
          name="color"
          title={translate("entities.marketRooms.inputColor.title")}
          placeholder={translate("entities.marketRooms.inputColor.placeholder")}
          initialColor={isEditing && tag.color}
        />
      </Box>
    </FormModal>
  );
}

TagModal.propTypes = tagModalPropTypes;

TagModal.defaultProps = tagModalDefaultProps;

export { TagModal, tagModalPropTypes, tagModalDefaultProps };
