import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import logo from "~/shared/assets/custom/logo.png";
import { useSocket, SocketProvider } from "~/modules/signed/hooks/webSocket";
import { useApp } from "~/shared/hooks/index";
import {
  GreenBackground,
  QuestionContainer,
  QuestionHeader,
  Title,
  QuestionMessage,
  Container,
} from "./styles";
import { chromaKey } from "~/shared/utils/variables";
import { isDataValid } from "~/shared/utils/validations";
import { Spinner } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
// APIS
import { getVariable } from "~/modules/signed/modules/client/apis/variables";

function Content() {
  const { roomId } = useParams();

  // REF
  const questionSocket = React.useRef(null);

  // STATES
  const { subscribe } = useSocket();
  const [question, setQuestion] = React.useState(null);
  const [isClosing, setIsClosing] = React.useState(false);
  const [color, setColor] = React.useState("#00f51f");
  const [isLoaded, setIsLoaded] = React.useState(false);

  // FUNCTIONS
  const { whatsAppButton, setWhatsAppButton } = useApp();
  const { translate } = useTranslation();

  async function getColor() {
    await getVariable({ key: chromaKey.COLOR }).then(response => {
      if (isDataValid(response.data.value)) setColor(response.data.value);
      setIsLoaded(true);
    });
  }

  React.useEffect(() => {
    getColor();
  }, []);

  React.useEffect(() => {
    setWhatsAppButton(null);

    questionSocket.current = subscribe(`question:${roomId}`);
    if (questionSocket.current)
      // Receive a question
      questionSocket.current.on("receive_selected_question", questionData => {
        if (questionData === "") {
          setIsClosing(true);

          setTimeout(() => {
            setIsClosing(false);
            setQuestion(questionData);
          }, 400);

          return;
        }

        setQuestion(questionData);
      });

    return () => {
      if (questionSocket.current) questionSocket.current.close();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [roomId, whatsAppButton, subscribe]);

  const Question = () => {
    if (!question) return null;

    return (
      <QuestionContainer isClosing={isClosing}>
        <QuestionHeader>
          <img src={logo} alt="logo" />
          <Title className="tinyTitle white" maxLines={1}>
            {translate("pages.questions.questionOf")}{" "}
            <strong>{question.user.name}</strong>
          </Title>
        </QuestionHeader>
        <QuestionMessage>{question.question}</QuestionMessage>
      </QuestionContainer>
    );
  };

  return (
    <>
      {!isLoaded ? (
        <Container>
          <Spinner centered="true" size="large" />
        </Container>
      ) : (
        <GreenBackground style={{ backgroundColor: color || "#00f51f" }}>
          <Question />
        </GreenBackground>
      )}
    </>
  );
}

function QuestionPreview() {
  return (
    <SocketProvider>
      <Content />
    </SocketProvider>
  );
}

export default QuestionPreview;
