import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import { blockUserFailed, blockUserSuccessful } from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// ACTIVITIES
export function* block({ payload }) {
  const { blockData } = payload;

  try {
    const response = yield call(api.post, `/admin/block`, blockData);

    if (validResponseStatus(response.status)) {
      yield put(blockUserSuccessful());
    }
  } catch (err) {
    console.log(err);
    yield put(blockUserFailed());
  }
}

export default all([takeLatest(types.BLOCK.REQUEST, block)]);
