import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography, Box, HoverWrapper } from "../../atoms";
import { Stars, Infos, CellList, infosPropTypes } from "../../molecules";

const evaluationContainerPropTypes = {
  title: PropTypes.string,
  subTitle: PropTypes.string,
  /** The initial rate  */
  rate: PropTypes.number,
  /** Rate title */
  rateTitle: PropTypes.string,
  /** Callback when user change the rate */
  onRateChange: PropTypes.func,
  /** Element that will be rendered at bottom of this component. */
  footerComponent: PropTypes.element,
  /** An array of <b>Infos</b>. See all props in <b>Molecules/Infos/b>. */
  infosData: PropTypes.arrayOf(
    PropTypes.shape({ id: PropTypes.number, ...infosPropTypes })
  ),
  /** If <b>true</b> the <b>Stars</b> component will start loading. */
  isRateLoading: PropTypes.bool,
  /** Callback when user clicks into some <b>Infos</b> in list.
   * return the clicked <b>Infos</b> data.
   *
   * Example: (infos) => {}
   */
  onInfoClick: PropTypes.func,
  /** If <b>true</b> hide <b>Stars</b> component. */
  hideRate: PropTypes.bool,
};

const evaluationContainerDefaultProps = {
  title: null,
  subTitle: null,
  rate: null,
  rateTitle: null,
  onRateChange: () => {},
  onInfoClick: null,
  footerComponent: null,
  infosData: [],
  isRateLoading: false,
  hideRate: false,
};

const EvaluationContainer = ({
  title,
  subTitle,
  rate,
  rateTitle,
  onRateChange,
  footerComponent,
  infosData,
  hideRate,
  onInfoClick,
  isRateLoading,
}) => {
  return (
    <Box>
      <Typography className="normal bold" maxLines={1}>
        {title}
      </Typography>
      {subTitle && (
        <Typography className="small opaque" maxLines={1}>
          {subTitle}
        </Typography>
      )}

      {!hideRate && (
        <Stars
          isLoaded={!isRateLoading}
          initialRate={rate}
          title={rateTitle}
          onClick={onRateChange}
          style={{ marginTop: 20 }}
        />
      )}

      <CellList
        isLoaded
        orientation="column"
        data={infosData}
        gap={8}
        listStyle={{ marginTop: 20 }}
        renderCell={info => (
          <HoverWrapper
            disableHoverEffect={!onInfoClick}
            variant="background"
            borderRadius={8}
            onClick={() => {
              if (onInfoClick) onInfoClick(info);
            }}
          >
            <Infos {...info} />
          </HoverWrapper>
        )}
      />

      {footerComponent}
    </Box>
  );
};

EvaluationContainer.propTypes = evaluationContainerPropTypes;

EvaluationContainer.defaultProps = evaluationContainerDefaultProps;

export {
  EvaluationContainer,
  evaluationContainerPropTypes,
  evaluationContainerDefaultProps,
};
