import React from "react";
import PropTypes from "prop-types";
import Textarea from "react-autosize-textarea";

// CUSTOM IMPORTS
import { getFullHours } from "~/shared/utils/dates";
import { PartnerLogoViewList } from "~/shared/components/organisms/lists";
import { Container, Schedule } from "./styles";

function InformationsFragment({ className, broadcastRoom }) {
  const { open_live: openLive, room, end_live: endLive } = broadcastRoom;

  return (
    <Container className={className}>
      <PartnerLogoViewList partners={room.partners} id="partner-logos" />

      <div className="transmission-room-times">
        {getFullHours(openLive)} -{getFullHours(endLive)}
      </div>

      <Textarea
        disabled
        className="transmission-room-description"
        defaultValue={room.description}
      />

      <Schedule broadcastRoomId={broadcastRoom.id} />
    </Container>
  );
}

InformationsFragment.propTypes = {
  className: PropTypes.string,
  broadcastRoom: PropTypes.object.isRequired,
};

InformationsFragment.defaultProps = {
  className: "room-infos-fragment",
};

export default InformationsFragment;
