/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import history from "~/shared/services/history";
import {
  listTagsSuccessful,
  createTagSuccessful,
  createTagFailed,
  updateTagSuccessful,
  updateTagFailed,
  deleteTagSuccessful,
  deleteTagFailed,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

export function* list({ payload }) {
  try {
    const { tagType } = payload;

    const response = yield call(api.get, `admin/tag/?type=${tagType}`);

    if (validResponseStatus(response.status)) {
      const tags = response.data;
      yield put(listTagsSuccessful(tags));
    }
  } catch (err) {
    console.log(err);
  }
}

export function* create({ payload }) {
  try {
    const { data } = payload;

    const response = yield call(api.post, "admin/tag", data);

    if (validResponseStatus(response.status)) {
      const tag = response.data;
      yield put(createTagSuccessful(tag));

      history.goBack();
    }
  } catch (err) {
    yield put(createTagFailed());

    console.log(err);
  }
}

export function* update({ payload }) {
  try {
    const { data, tagId } = payload;

    const response = yield call(api.put, `admin/tag/${tagId}`, data);

    if (validResponseStatus(response.status)) {
      const tag = response.data;

      yield put(updateTagSuccessful(tag));

      history.goBack();
    }
  } catch (err) {
    yield put(updateTagFailed());

    console.log(err);
  }
}

export function* destroy({ payload }) {
  try {
    const { tagId } = payload;

    const response = yield call(api.delete, `admin/tag/${tagId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteTagSuccessful(tagId));

      history.goBack();
    }
  } catch (err) {
    yield put(deleteTagFailed());

    console.log(err);
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
]);
