import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { NotificationCard } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

const defaultNotificationPropTypes = {
  notification: PropTypes.shape({
    description: PropTypes.string,
    icon: PropTypes.string,
    link: PropTypes.string,
    title: PropTypes.string,
    user_connection_id: PropTypes.number,
    onClick: PropTypes.func,
  }).isRequired,

  // translate: PropTypes.func.isRequired,
};

const AUTO_CLOSE_INITIAL_VALUE = 10000;

const DefaultNotification = ({ notification, ...rest }) => {
  const { translate } = useTranslation();
  const { onClick } = notification;

  return (
    <NotificationCard
      onClick={() => {
        if (typeof onClick === "function") onClick();
      }}
      icon={notification.icon}
      variant="popup"
      title={notification.title}
      description={notification.description}
      time={translate("common.now")}
      autoClose={AUTO_CLOSE_INITIAL_VALUE}
      link={notification.link}
      unreadLink={translate("common.clickToAccess")}
      {...rest}
    />
  );
};

DefaultNotification.propTypes = defaultNotificationPropTypes;

export { DefaultNotification };
