import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, CustomSpinner } from "./styles";

const spinnerPropTypes = {
  /** if TRUE create a div around the spinner that fills the entire space and centralizes the spinner */
  centered: PropTypes.bool,
  size: PropTypes.oneOf(["small", "regular", "large"]),
  color: PropTypes.string,
  style: PropTypes.object,
};

const spinnerDefaultProps = {
  centered: false,
  size: "regular",
  color: null,
  style: null,
};

/** Spinner commonly used to when something is loading */
function Spinner({ centered, size, color, style, ...rest }) {
  //* FUNCTIONS
  const renderSize = () => {
    switch (size) {
      case "small":
        return 20;

      case "regular":
        return 30;

      case "large":
        return 40;

      default:
        return 30;
    }
  };

  return centered ? (
    <Container centered={centered} style={style}>
      <CustomSpinner
        customColor={color}
        color="inherit"
        size={renderSize()}
        {...rest}
      />
    </Container>
  ) : (
    <CustomSpinner
      customColor={color}
      color="inherit"
      size={renderSize()}
      style={style}
      {...rest}
    />
  );
}

Spinner.propTypes = spinnerPropTypes;
Spinner.defaultProps = spinnerDefaultProps;

export { Spinner, spinnerPropTypes, spinnerDefaultProps };
