import produce from "immer";

import types from "./types";

const INITIAL_STATE = {
  block: {
    isBlocking: false,
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case types.BLOCK.REQUEST: {
        draft.block.isBlocking = true;
        break;
      }
      case types.BLOCK.SUCCESSFUL:
      case types.BLOCK.FAILED: {
        draft.block.isBlocking = false;
        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.block.isBlocking = false;
        break;
      }
      default:
    }
  });
}
