import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useExtractPoints } from "./hooks/useExtractPoints";
import { Log } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { Empty } from "~/shared/components/molecules";
import { Icon } from "~/shared/components/atoms";

const extractPointsLogPropTypes = {
  pointsLog: PropTypes.shape({
    data: PropTypes.array,
    page: PropTypes.number,
    lastPage: PropTypes.number,
    isLoaded: PropTypes.bool,
  }).isRequired,
  /** Callback when users paginate  */
  onPaginate: PropTypes.func.isRequired,
  /** The maxHeight of scroll in string styles.
   *
   * Exampla: "500px"
   */
  maxHeight: PropTypes.string.isRequired,
};

const ExtractPointsLog = ({ pointsLog, onPaginate, maxHeight, ...rest }) => {
  const { extractPoints } = useExtractPoints(pointsLog);
  const { translate } = useTranslation();

  return (
    <Log
      {...rest}
      isLoading={!pointsLog.isLoaded}
      data={extractPoints}
      loaderListSize={9}
      disableScrollListener={pointsLog.page >= pointsLog.lastPage}
      onBottomReached={onPaginate}
      orientation="column"
      listStyle={{ maxHeight, padding: "0 8px" }}
      withScroll
      emptyComponent={
        <Empty
          icon={<Icon iconName="gamification" />}
          message={translate("entities.gamification.userExtract.empty.message")}
          description={translate(
            "entities.gamification.userExtract.empty.description"
          )}
        />
      }
    />
  );
};

ExtractPointsLog.propTypes = extractPointsLogPropTypes;

export { ExtractPointsLog };
