//* CUSTOM IMPORTS
import { USER_TYPES } from "~/shared/utils/user";
import {
  openAdminDashboard,
  openAdminBroadcastRooms,
} from "../modules/admin/routes/navigation";
import { openSponsorDashboard } from "../modules/sponsor/routes/navigation";

export * from "~/modules/signed/modules/cms/modules/sponsor/routes/navigation";
export * from "~/modules/signed/modules/cms/modules/admin/routes/navigation";

export const openCMSDashboard = accessType => {
  const { ADMIN, SPONSOR, EDITOR } = USER_TYPES;

  switch (accessType) {
    case ADMIN.value:
      openAdminDashboard();
      break;

    case EDITOR.value:
      openAdminBroadcastRooms();
      break;

    case SPONSOR.value:
      openSponsorDashboard();
      break;

    default:
      openAdminDashboard();
      break;
  }
};
