import styled, { css } from "styled-components";

export const Container = styled.span`
  position: relative;
  cursor: pointer;
`;

export const Tip = styled.span`
  box-shadow: 0 2px 5px ${props => props.theme.colors.shadow};
  opacity: ${props => (props.visibility ? 1 : 0)};
  visibility: ${props => (props.visibility ? "visible" : "hidden")};
  transition: opacity 0.4s ease, transform 0.4s ease-in;
  min-width: 200px;

  ${props => {
    switch (props.variant) {
      case "default":
        return css`
          display: flex;
          justify-content: center;
          text-align: center;
          background: ${props.color ? props.color : props.theme.colors.accent};
          padding: 8px;
          font-size: 14px;
          font-weight: bold;
          border-radius: 8px;

          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          bottom: calc(100% + 4px);
          color: #fff;

          &::before {
            content: "";
            border-style: solid;
            border-color: ${props.color
                ? props.color
                : props.theme.colors.accent}
              transparent;
            border-width: 6px 6px 0 6px;
            top: 100%;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
          }
        `;

      case "contained":
        return css`
          overflow-x: hidden;
          overflow-y: auto;
          max-height: 100px;
          position: absolute;
          right: 50%;
          bottom: 50%;
          transform: translate(0, 50%);
          padding: 16px;
          border-radius: 8px;
          text-align: end;

          background: ${props.theme.colors.surface};
          padding: 16px;
          font-size: 12px;
          font-weight: bold;

          strong {
            color: ${props.theme.colors.accent};
          }
        `;

      default:
        break;
    }
  }}

  span {
    display: flex;
    justify-content: center;
    text-align: center;
    min-width: 200px;
    background: ${props =>
      props.color ? props.color : props.theme.colors.accent};
    padding: 8px;
    font-size: 14px;
    font-weight: bold;
    border-radius: 8px;
    box-shadow: 0 2px 5px ${props => props.theme.colors.shadow};
    opacity: ${props => (props.visibility ? 1 : 0)};
    transition: opacity 0.4s ease;

    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(100% + 4px);
    color: #fff;

    &::before {
      content: "";
      border-style: solid;
      border-color: ${props =>
          props.color ? props.color : props.theme.colors.accent}
        transparent;
      border-width: 6px 6px 0 6px;
      top: 100%;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
`;
