import React from "react";

//* APIs
import { deleteLanguage } from "../../../../modules/admin/apis/languages";

//* CUSTOM IMPORTS
import TranslateLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import {
  openAdminCreateLanguage,
  openAdminEditLanguage,
} from "~/shared/routes/navigation";
import { LanguagesMenu } from "~/shared/components/molecules";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const CMSTranslationMenu = ({ style, ...rest }) => {
  const {
    removeLanguage,
    changeLanguage,
    currentLanguage,
    fillLanguagesMenu,
    getLangIdByRefCode,
    isLangLoaded,
  } = useDynamicTranslation();

  //* FUNCTIONS
  const handleOnFlagDelete = async flag => {
    if (flag)
      await deleteLanguage({ languageId: getLangIdByRefCode(flag.id) }).then(
        () => {
          removeLanguage(getLangIdByRefCode(flag.id));
        }
      );
  };

  const handleFlagClick = flag => {
    if (flag.id !== currentLanguage.refCode)
      changeLanguage({ ...flag, refCode: flag.id });
  };

  return isLangLoaded ? (
    <LanguagesMenu
      {...rest}
      selectedFlagId={currentLanguage.refCode}
      buttonCountryCode={currentLanguage.countryCode}
      onFlagClick={handleFlagClick}
      onAddFlag={openAdminCreateLanguage}
      onFlagDelete={flag => handleOnFlagDelete(flag)}
      onFlagEdit={flag =>
        openAdminEditLanguage({ languageid: getLangIdByRefCode(flag.id) })
      }
      data={fillLanguagesMenu()}
      style={style}
    />
  ) : (
    <TranslateLoader
      style={{ height: 25, width: 40, justifyContent: "unset", ...style }}
      height={25}
      width={40}
    />
  );
};

export { CMSTranslationMenu };
