import React from "react";

// APIs
import {
  listPosters,
  deletePoster,
} from "~/modules/signed/modules/cms/modules/admin/apis/posters";

// CUSTOM IMPORTS
import { deleteItem } from "~/shared/utils/lists";
import { isListValid } from "~/shared/utils/validations";
import { CellList } from "~/shared/components/molecules";
import { OutlinedButton } from "~/shared/components/atoms";
import { PosterCell } from "~/shared/components/organisms/cells";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { openCreatePoster, openEditPoster } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { Container, SearchBar } from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function Posters() {
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  // STATES
  const [posters, setPosters] = React.useState({
    total: 0,
    perPage: 0,
    page: 0,
    lastPage: 0,
    data: [],
  });
  const [isSearching, setIsSearching] = React.useState(false);
  const [search, setSearch] = React.useState("");
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isDeleting, setIsDeleting] = React.useState(false);
  const [cellId, setCellId] = React.useState(null);
  const [hasNextPage, setHasNextPage] = React.useState(true);
  const [isPaging, setIsPaging] = React.useState(false);

  // FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      setIsSearching(true);

      await listPosters({ search }).then(response => {
        setPosters(response.data);
        setIsLoaded(true);
      });

      setHasNextPage(true);
      setIsSearching(false);
    };

    fetch();
  }, [search]);

  const onSearchChange = async value => {
    setSearch(value);
  };

  const onPagination = async () => {
    if (!hasNextPage) return;

    setIsPaging(true);

    await listPosters({ page: posters.page + 1, search }).then(response => {
      const { data, lastPage, page, perPage, total } = response.data;

      if (isListValid(response.data.data)) {
        setPosters({
          data: [...posters.data, ...data],
          lastPage,
          page,
          perPage,
          total,
        });
      }

      if (response.data.data.length !== perPage) setHasNextPage(false);
    });

    setIsPaging(false);
  };

  const onCellDelete = async posterId => {
    setIsDeleting(true);
    setCellId(posterId);

    await deletePoster({ posterId })
      .then(() => {
        setPosters(deleteItem({ itemId: posterId, list: posters }));
        setIsDeleting(false);
      })
      .catch(() => setIsDeleting(false));
  };

  const renderAddButton = () => (
    <OutlinedButton
      title={translate("entities.posters.add.title")}
      placeholder={translate("entities.posters.add.placeholder")}
      style={{
        minWidth: 302,
        maxWidth: 300,
        height: 162,
      }}
      onClick={openCreatePoster}
    />
  );

  const renderCell = poster => {
    const { title, description, author, co_author: coAuthors } = poster;

    const coAuthorsSize = !coAuthors ? [] : String(coAuthors).split(",");

    return (
      <PosterCell
        entity={translate("entities.posters.singular")}
        entityName={dTranslate(title)}
        title={dTranslate(title)}
        footerDescription={`${translate("common.per")} ${author}  ${
          coAuthorsSize.length > 0
            ? `${translate("common.andMore")} ${coAuthorsSize.length}`
            : ""
        }`}
        description={dTranslate(description)}
        onClick={() => openEditPoster(poster.id)}
        onEdit={() => openEditPoster(poster.id)}
        onDelete={() => onCellDelete(poster.id)}
      />
    );
  };

  return (
    <Container>
      <SearchBar
        isSearching={isSearching}
        placeholder={translate("entities.posters.search.placeholder")}
        onChange={onSearchChange}
      />

      <CellList
        wrap
        isPaging={isPaging}
        firstCell={renderAddButton()}
        cellId={cellId}
        isDeleting={isDeleting}
        isLoaded={isLoaded}
        data={posters.data}
        renderCell={renderCell}
        loaderListSize={9}
        renderCellLoader={() => (
          <CellLoader width={298} height={160} style={{ width: 298 }} />
        )}
        gap={20}
        withScroll
        onBottomReached={onPagination}
        scrollStyle={{ maxHeight: "calc(100vh - 240px)" }}
      />
    </Container>
  );
}

export default Posters;
