import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Typography,
  Box,
  CircularProgress,
  circularProgressPropTypes,
  circularProgressDefaultProps,
} from "../../atoms";
import SingleBarLoader from "../../molecules/cellLoaders/SingleBarLoader";
import CircleLoader from "../../molecules/cellLoaders/CircleLoader";

const importStatusTemplatePropTypes = {
  title: PropTypes.string.isRequired,

  subTitle: PropTypes.string,

  children: PropTypes.element.isRequired,

  /** Implement this props to Control the <b>CircularProgress</b> component,
   * see all props in <b>Atom/CircularProgress
   */
  circularProgress: PropTypes.shape(circularProgressPropTypes),

  /** Render an Loader Container instead of the <ImportStatusTemplate>ImportStatusTemplate</b> */
  isLoading: PropTypes.bool,
};

const importStatusTemplateDefaultProps = {
  subTitle: null,
  circularProgress: circularProgressDefaultProps,
  isLoading: false,
};

/** ImportStatusTemplate is commonly used in import pages, to display the current
 * import status
 */
const ImportStatusTemplate = ({
  title,
  subTitle,
  children,
  isLoading,
  circularProgress,
  ...rest
}) => {
  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      flex="1"
      {...rest}
    >
      {isLoading ? (
        <>
          <SingleBarLoader width={350} />

          <SingleBarLoader style={{ marginTop: 16 }} />

          <CircleLoader style={{ margin: 40 }} />

          <SingleBarLoader width={150} />

          <SingleBarLoader style={{ marginTop: 16 }} />

          <SingleBarLoader width={250} style={{ marginTop: 16 }} />
        </>
      ) : (
        <>
          <Typography className="bigTitle normal">{title}</Typography>

          {subTitle && (
            <Typography className="subTitle" style={{ marginTop: 8 }}>
              {subTitle}
            </Typography>
          )}

          {circularProgress && (
            <CircularProgress
              {...circularProgress}
              size={80}
              style={{ marginTop: 40 }}
            />
          )}

          <Box marginTop="40px">{children}</Box>
        </>
      )}
    </Box>
  );
};

ImportStatusTemplate.propTypes = importStatusTemplatePropTypes;
ImportStatusTemplate.defaultProps = importStatusTemplateDefaultProps;

export {
  ImportStatusTemplate,
  importStatusTemplatePropTypes,
  importStatusTemplateDefaultProps,
};
