import scheduleTypes from "./types";

// LIST BROADCAST ROOM SCHEDULE
export function listBroadcastRoomSchedule(broadcastRoomId) {
  return {
    type: "@schedules/LIST_BROADCAST_ROOM_SCHEDULE",
    payload: { broadcastRoomId },
  };
}
export function listBroadcastRoomScheduleSuccessful(schedules) {
  return {
    type: "@schedules/LIST_BROADCAST_ROOM_SCHEDULE_SUCCESSFUL",
    payload: { schedules },
  };
}

// CREATE
export function createSchedule(schedule, broadcastRoomId) {
  return {
    type: scheduleTypes.CREATE.REQUEST,
    payload: { schedule, broadcastRoomId },
  };
}
export function createScheduleSuccessful(schedule) {
  return {
    type: scheduleTypes.CREATE.SUCCESSFUL,
    payload: { schedule },
  };
}
export function createScheduleFailed() {
  return {
    type: scheduleTypes.CREATE.FAILED,
  };
}

// UPDATE
export function updateSchedule(schedule, broadcastRoomId, scheduleId) {
  return {
    type: scheduleTypes.UPDATE.REQUEST,
    payload: { schedule, broadcastRoomId, scheduleId },
  };
}
export function updateScheduleSuccessful(schedule) {
  return {
    type: scheduleTypes.UPDATE.SUCCESSFUL,
    payload: { schedule },
  };
}
export function updateScheduleFailed() {
  return {
    type: scheduleTypes.UPDATE.FAILED,
  };
}

// DELETE
export function deleteSchedule(scheduleId) {
  return {
    type: scheduleTypes.DELETE.REQUEST,
    payload: { scheduleId },
  };
}
export function deleteScheduleSuccessful(scheduleId) {
  return {
    type: scheduleTypes.DELETE.SUCCESSFUL,
    payload: { scheduleId },
  };
}
export function deleteScheduleFailed() {
  return {
    type: scheduleTypes.DELETE.FAILED,
  };
}

// FORM
export function clearForm() {
  return {
    type: "@schedules/CLEAR_FORM",
  };
}

// CLEAR LIST
export function clearSchedules() {
  return {
    type: "@schedules/CLEAR",
  };
}
