import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  listBanners,
  setBanners,
  reorderBanners,
} from "~/modules/signed/modules/cms/modules/admin/store/banners/actions";

// CUSTOM IMPORTS
import { Trans } from "~/shared/components/atoms";
import Loader from "~/shared/components/molecules/cellLoaders/FileInputLoader";
import { Container, AddButton, ContentLoader } from "./styles";
import { DraggableList } from "~/shared/components/molecules";
import BannerCell from "~/modules/signed/modules/cms/components/cells/BannerCell";

function BannerList({ className, onEdit, onAddButton }) {
  const dispatch = useDispatch();
  const { data: banners, isLoaded, bannerId, isUpdating } = useSelector(
    state => state.adminBanners.list
  );

  // FUNCTIONS
  React.useEffect(() => {
    if (!isLoaded) dispatch(listBanners());
  }, [dispatch, isLoaded]);

  function handleSortList(sortedList) {
    const reorderedList = sortedList.map((field, index) => {
      return {
        id: Number(field.id),
        order: index,
      };
    });

    dispatch(setBanners({ banners: sortedList }));
    dispatch(reorderBanners({ reorderedList }));
  }

  function handleOnEdit(banner) {
    onEdit(banner);
  }

  return isLoaded ? (
    <Container className={className}>
      <AddButton
        direction="row"
        onClick={onAddButton}
        placeholder={
          <Trans translationKey="entities.banners.add">Adicionar Banner</Trans>
        }
      />

      <DraggableList
        data={banners}
        onDragFinish={handleSortList}
        renderCell={item =>
          bannerId === item.id && isUpdating === "PENDING" ? (
            <Loader width={423} />
          ) : (
            <BannerCell banner={item} onEdit={handleOnEdit} />
          )
        }
      />
    </Container>
  ) : (
    <ContentLoader size={4} cellLoader={<Loader width={423} />} />
  );
}

BannerList.propTypes = {
  className: PropTypes.string.isRequired,
  onEdit: PropTypes.func.isRequired,
  onAddButton: PropTypes.func.isRequired,
};

export default BannerList;
