import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Input,
  LinkButton,
  CountryIcon,
  countryIconPropTypes,
} from "../../atoms";
import { LanguagesMenu, languagesMenuPropTypes } from "../../molecules";
import { useTranslation } from "../../../hooks/translation";
import { Container } from "./styles";

const translationInputPropTypes = {
  className: PropTypes.string,
  style: PropTypes.object,

  /** If this props has some data, add a <b>LanguageMenu</b> bellow the component,
   * to see all props check <b>Molecules/LanguageMenu</b>
   */
  languageMenu: PropTypes.shape(languagesMenuPropTypes),

  /** If this props has data, add an <b>CountryIcon</b> inside the <b>Input</b> */
  countryCode: countryIconPropTypes.countryCode,

  /** All HTMLInputTypes */
  type: PropTypes.string,
};

const translationDefaultProps = {
  className: null,
  style: null,
  languageMenu: null,
  countryCode: null,
  type: "text",
};

/** TranslationInput extends the <b>Input</b> component, too see all props details
 * check the <b>Atoms/Input</b> component.
 */
const TranslationInput = ({
  className,
  style,
  languageMenu,
  countryCode,
  type,
  ...rest
}) => {
  const { translate } = useTranslation();

  const disableTranslation =
    type === "number" || type === "date" || type === "time";

  return (
    <Container className={className} style={style}>
      <Input
        {...rest}
        type={type}
        icon={
          countryCode &&
          !disableTranslation && <CountryIcon countryCode={countryCode} />
        }
      />

      {languageMenu && !disableTranslation && (
        <LanguagesMenu
          {...languageMenu}
          menuButton={
            <LinkButton>{translate("common.addTranslation")}</LinkButton>
          }
        />
      )}
    </Container>
  );
};

TranslationInput.propTypes = translationInputPropTypes;
TranslationInput.defaultProps = translationDefaultProps;

export { TranslationInput };
