import React from "react";
import { useSelector } from "react-redux";

//* CUSTOM IMPORTS
import { pushState } from "~/shared/services/history";
import { openBroadcastRooms } from "~/shared/routes/navigation";
import { NotFoundTemplate } from "~/shared/components/templates";

const Page404 = () => {
  const { signed } = useSelector(state => state.auth);

  return (
    <NotFoundTemplate
      message="Página não encontrada"
      description="Não encontramos a página que você procurava"
      button={{
        onClick: signed ? openBroadcastRooms : pushState("/login"),
        children: signed ? "Voltar ao ínicio" : "Voltar ao login",
      }}
    />
  );
};

export default Page404;
