import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { unshitItem, updateItem, deleteItem } from "~/shared/utils/lists";

//* APIS
import {
  listEmails as callListEmails,
  createEmail,
  deleteEmail,
  updateEmail,
  showEmail,
  emailLog,
  emailLogCount,
  checkEmailVerified,
} from "~/modules/signed/modules/cms/modules/admin/apis/emails";
import { getVariable } from "~/modules/signed/modules/cms/modules/admin/apis/variables";

//* CUSTOM IMPORTS
import { EMAIL_TYPES } from "~/shared/utils/types";

const EmailContext = createContext({});

const CHECK_STATUS = {
  IDLE: 1,
  VERIFIED: 2,
  NOT_VERIFIED: 3,
};

const EMAILS_INITIAL_DATA = {
  data: [],
  isLoaded: false,
  page: 1,
  lastPage: null,
  isPaging: false,
  isDeleting: false,
};

const AdminEmailProvider = ({ children }) => {
  //* STATES
  const [sendedEmails, setSendedEmails] = React.useState(EMAILS_INITIAL_DATA);
  const [scheduleEmails, setScheduleEmails] = React.useState(
    EMAILS_INITIAL_DATA
  );
  const [sketchEmails, setSketchEmails] = React.useState(EMAILS_INITIAL_DATA);
  const [email, setEmail] = React.useState(null);
  const [sender, setSender] = React.useState({
    email: null,
    name: null,
  });
  const [emailVerifyStatus, setEmailVerifyStatus] = React.useState(
    CHECK_STATUS.IDLE
  );
  const [log, setLog] = React.useState(null);
  const [logCount, setLogCount] = React.useState();
  const [isEmailLoaded, setIsEmailLoaded] = React.useState(false);
  const [isLogLoaded, setIsLogLoaded] = React.useState(false);

  //* FUNCTIONS
  const listEmails = React.useCallback((page, type, setState) => {
    const list = async () => {
      setState(value => ({ ...value, isPaging: true }));

      await callListEmails({ page, type }).then(response => {
        setState(value => {
          return {
            data: [...value.data, ...response.data.data],
            isLoaded: true,
            page: response.data.page,
            lastPage: response.data.lastPage,
            isPaging: false,
          };
        });
      });

      setState(value => ({ ...value, isPaging: false }));
    };

    list();
  }, []);

  const setEmails = React.useCallback((type, callback) => {
    switch (type) {
      case EMAIL_TYPES.SKETCH:
        return setSketchEmails(value => callback(value));

      case EMAIL_TYPES.SCHEDULE:
        return setScheduleEmails(value => callback(value));

      default:
        return setSendedEmails(value => callback(value));
    }
  }, []);

  React.useEffect(() => {
    const get = async () => {
      await getVariable({ key: EMAIL_TYPES.SENDER }).then(response =>
        setSender(JSON.parse(response.data.value))
      );
    };
    get();
  }, []);

  React.useEffect(() => {
    listEmails(sketchEmails.page, EMAIL_TYPES.SKETCH, setSketchEmails);
  }, [listEmails, sketchEmails.page]);

  React.useEffect(() => {
    listEmails(scheduleEmails.page, EMAIL_TYPES.SCHEDULE, setScheduleEmails);
  }, [listEmails, scheduleEmails.page]);

  React.useEffect(() => {
    listEmails(sendedEmails.page, EMAIL_TYPES.SENDED, setSendedEmails);
  }, [listEmails, sendedEmails.page]);

  const clearAdminEmail = React.useCallback(() => {
    setEmail(null);
    setIsEmailLoaded(false);
    setEmailVerifyStatus(CHECK_STATUS.IDLE);
  }, []);

  const clearAdminLog = React.useCallback(() => {
    setLog(null);
    setIsLogLoaded(false);
  }, []);

  const paginateEmails = React.useCallback(
    type => {
      setEmails(type, value => ({
        ...value,
        page: value.page + 1,
      }));
    },
    [setEmails]
  );

  const createAdminEmail = React.useCallback(
    emailData => {
      return new Promise(resolve => {
        createEmail({ data: emailData }).then(response => {
          setEmails(emailData.type, value => ({
            ...value,
            data: unshitItem({ newItem: response.data, list: value.data }),
          }));

          resolve(response);
        });
      });
    },
    [setEmails]
  );

  const showAdminEmail = React.useCallback(id => {
    const fetch = async () => {
      await showEmail({ id }).then(response => {
        setEmail(response.data);
        setIsEmailLoaded(true);
      });
    };
    fetch();
  }, []);

  const deleteAdminEmail = React.useCallback(
    (emailId, emailType) => {
      const fetch = async () => {
        setEmails(emailType, value => ({ ...value, isDeleting: true }));

        await deleteEmail({ id: emailId }).then(() => {
          setEmails(emailType, value => ({
            ...value,
            data: deleteItem({ itemId: emailId, list: value.data }),
          }));
        });

        setEmails(emailType, value => ({ ...value, isDeleting: false }));
      };

      fetch();
    },
    [setEmails]
  );

  const updateAdminEmail = React.useCallback(
    (emailData, emailId, emailType) => {
      return new Promise(resolve => {
        updateEmail({ data: emailData, id: emailId }).then(response => {
          const { type } = response.data;
          if (emailType === type) {
            setEmails(emailData.type, value => ({
              ...value,
              data: updateItem({
                updatedItem: response.data,
                list: value.data,
              }),
            }));
          } else {
            setEmails(emailType, value => ({
              ...value,
              data: deleteItem({ itemId: emailId, list: value.data }),
            }));
            setEmails(type, value => ({
              ...value,
              data: unshitItem({ newItem: response.data, list: value.data }),
            }));
          }

          resolve(response);
        });
      });
    },
    [setEmails]
  );

  const adminEmailLog = React.useCallback((id, page) => {
    const fetch = async () => {
      await emailLog({ id, page }).then(response => {
        setLog(response.data);
        setIsLogLoaded(true);
      });
    };
    fetch();
  }, []);

  const adminEmailLogCount = React.useCallback(id => {
    const fetch = async () => {
      await emailLogCount({ id }).then(response => {
        setLogCount(response.data);
      });
    };
    fetch();
  }, []);

  /** This function will check if the email in params is verified. */
  const checkEmail = React.useCallback(unverifiedEmail => {
    return new Promise(resolve => {
      checkEmailVerified({
        email: unverifiedEmail,
      }).then(response => {
        const status = response.data.email
          ? CHECK_STATUS.VERIFIED
          : CHECK_STATUS.NOT_VERIFIED;

        setEmailVerifyStatus(status);

        resolve(status);
      });
    });
  }, []);

  return (
    <EmailContext.Provider
      value={{
        sendedEmails,
        scheduleEmails,
        sketchEmails,
        email,
        log,
        sender,
        logCount,
        isLogLoaded,
        isEmailLoaded,
        emailVerifyStatus,
        CHECK_STATUS,
        checkEmail,
        createAdminEmail,
        deleteAdminEmail,
        updateAdminEmail,
        showAdminEmail,
        adminEmailLog,
        clearAdminEmail,
        clearAdminLog,
        adminEmailLogCount,
        paginateEmails,
      }}
    >
      {children}
    </EmailContext.Provider>
  );
};

AdminEmailProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useAdminEmail = () => {
  const context = useContext(EmailContext);

  if (!context)
    throw new Error("useAdminEmail must be used within AdminEmailProvider");

  return context;
};

export { AdminEmailProvider, useAdminEmail };
