import api from "~/shared/services/api";

export const createBroadcastRoomScheduleRate = ({ rate, scheduleId }) =>
  Promise.resolve(
    api.post(`/rateroom`, {
      ref_id: scheduleId,
      rate,
      type: "schedule",
    })
  );

export const showBroadcastRoomScheduleRate = ({ scheduleId, roomId }) =>
  Promise.resolve(
    api.get(`/rateroom/schedule`, {
      params: { room_id: roomId, ref_id: scheduleId },
    })
  );
