import styled from "styled-components";

export const TableData = styled.div`
  display: flex;
  align-items: center;
  border-bottom: 1px solid ${props => props.theme.colors.smoked};
  padding: 24px 64px 24px 0;
  width: ${props => props.defaultWidth}px;
  height: 70px;
  position: relative;

  input {
    border: none;
    background: none;
    font-weight: ${props => (props.isErrored ? "bold" : "normal")};
    font-style: ${props => (props.isEmpty ? "italic" : "normal")};

    color: ${props => {
      const { isErrored, isEmpty } = props;

      if (isErrored) return props.theme.colors.redWrong;

      if (isEmpty) return props.theme.colors.opaque;

      return props.theme.colors.normal;
    }};
  }
`;

export const TableDataLoader = styled(TableData)``;
