import styled, { css } from "styled-components";
import { shade } from "polished";

//* CUSTOM IMPORTS
import { Typography, IconButton } from "../../atoms";

export const Container = styled.div`
  background: ${props => shade(0.03, props.theme.colors.surface)};
  border-radius: 4px;
  border-left: 5px solid ${props => props.color || props.theme.colors.accent};
  padding: 10px;
  padding-left: 30px;
  position: relative;

  ${props => {
    switch (props.variant) {
      case "shadeAccent":
        return css`
          color: ${props.theme.colors.surface};
          background-color: ${shade(0.05, props.theme.colors.accent)};
          border-left: 5px solid ${props.theme.colors.surface};
        `;

      default:
        break;
    }
  }}
`;

export const Message = styled(Typography)`
  word-break: break-word;
  ${props =>
    props.hasHighlight &&
    css`
      strong {
        background: ${props.theme.colors.accent};
        color: ${props.theme.colors.surface};
      }
    `}
`;

export const CloseButton = styled(IconButton)`
  position: absolute;
  top: -8px;
  right: -8px;

  svg {
    height: 14px;
    width: 14px;
  }
`;
