import React from "react";
import { useParams } from "react-router-dom";

//* CUSTOM IMPORTS
import { getUserImportLog } from "../../../../../../apis/users";
import { useCMS } from "../../../../../../../../hooks";
import {
  IMPORT_STATUS,
  useImportSocket,
} from "../../../../../../hooks/webSocket/channels/import";
import {
  openAdminUsers,
  openAdminUserImport,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { LinkButton, Typography } from "~/shared/components/atoms";
import { ImportStatusTemplate } from "~/shared/components/templates";
import { ImportErrorsModal } from "~/shared/components/organisms";
import { useImportUsers } from "../../hooks";
import { Content } from "./styles";

const ImportUsersStatus = () => {
  const { setBreadCrumbs, setFooterButtons } = useCMS();
  const { translate } = useTranslation();
  const { importId } = useParams();
  const { onImportFinished, onImportProgress } = useImportSocket();
  const {
    cadasterItems,
    errorsImportMessageToString,
    updateImportLogStatus,
  } = useImportUsers();

  //* STATES
  const [showErrorModal, setShowErrorModal] = React.useState(false);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [importStatusData, setImportStatusData] = React.useState(null);
  const [errorsImport, setErrorsImport] = React.useState([]);
  const [progress, setProgress] = React.useState(0);

  //* FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      await getUserImportLog({ importId }).then(response => {
        const { report_data: reportData, progress: p, status } = response.data;

        setImportStatusData(response.data);
        setProgress(status === IMPORT_STATUS.FINISHED ? 100 : p);

        //* *If has errors import populate the state */
        if (reportData) setErrorsImport(reportData.errors);

        setIsLoaded(true);
      });
    };

    if (importId) fetch();
  }, [importId]);

  React.useMemo(() => {
    setProgress(onImportProgress(importId));
  }, [importId, onImportProgress]);

  React.useEffect(() => {
    setFooterButtons(null);

    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.users.plural"),
        function: openAdminUsers,
      },
      {
        id: 2,
        name: translate("entities.users.import"),
        function: openAdminUserImport,
      },
      {
        id: 3,
        name: importStatusData && importStatusData.file_name,
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [importStatusData, setBreadCrumbs, setFooterButtons, translate]);

  /** If status is not FINISHED listen the onImportFinished function
   * thats return the value of the importStatus when it is finished
   */
  React.useMemo(() => {
    if (!importStatusData) return;

    const { status } = importStatusData;

    if (status === IMPORT_STATUS.STARTED) {
      const importStatus = onImportFinished(importId);

      if (importStatus) {
        setImportStatusData(importStatus);
        updateImportLogStatus(importId, IMPORT_STATUS.FINISHED);
      }
    }
  }, [importStatusData, updateImportLogStatus, importId, onImportFinished]);

  const renderContent = () => {
    if (!importStatusData) return null;

    const { report_data: reportData, configs, status } = importStatusData;

    if (status !== IMPORT_STATUS.FINISHED) return null;

    if (!reportData) return null;

    const { successCount, errorCount } = reportData;

    return (
      <Content>
        <Typography style={{ marginBottom: 16 }}>
          <strong>
            {successCount === 0 ? translate("common.none") : successCount}
          </strong>{" "}
          {translate(
            successCount > 1
              ? "entities.users.usersHaveBeenImported"
              : "entities.users.userHaveBeenImported"
          )}
        </Typography>

        {errorCount > 0 && (
          <LinkButton
            onClick={() => setShowErrorModal(true)}
            variant="error"
            style={{ marginBottom: 16 }}
          >
            <strong>{errorCount}</strong>{" "}
            {translate(
              errorCount > 1
                ? "entities.users.usersHaveNotBeenImported"
                : "entities.users.userHaveNotBeenImported"
            )}
            {" - "}
            {translate(
              errorCount > 1
                ? "entities.users.viewUsers"
                : "entities.users.viewUser"
            )}
          </LinkButton>
        )}

        {configs && configs.send_email && successCount > 0 && (
          <Typography style={{ marginBottom: 16 }}>
            <strong>{translate("common.anEmailWasSent")}</strong>{" "}
            {translate("common.withTheInstructions")}
          </Typography>
        )}

        {errorsImport.length > 0 && (
          <ImportErrorsModal
            show={showErrorModal}
            title={translate("entities.users.withErrors")}
            titles={cadasterItems.map(item => item.title)}
            data={errorsImport.map(errorImport => {
              const { errors, index, user_structure: user } = errorImport;

              const row = cadasterItems.map(item => {
                const rowValue = Object.entries(user).find(
                  entry => entry[0] === item.ref_code
                );

                return {
                  value: rowValue ? rowValue[1] : null,
                  error: errors
                    ? !!errors.find(error => error.field === item.ref_code)
                    : false,
                  lineErrorIndex: index,
                  refCode: item.ref_code,
                };
              });

              return row;
            })}
            errorMessages={errorsImportMessageToString(errorsImport)}
            onClose={() => {
              setShowErrorModal(false);
            }}
            onSave={() => {}}
            emptyValueComponent={
              <Typography className="opaque italic">
                {translate("common.unavailable")}
              </Typography>
            }
          />
        )}
      </Content>
    );
  };

  const renderTitle = () => {
    if (!importStatusData) return null;

    const { status } = importStatusData;

    if (status === IMPORT_STATUS.STARTED) return "Importação em andamento";

    if (status === IMPORT_STATUS.FINISHED) return "Importação concluída";

    return null;
  };

  const renderSubTitle = () => {
    if (!importStatusData) return null;

    const { status } = importStatusData;

    if (status === IMPORT_STATUS.STARTED)
      return translate("pages.userImportStatus.importPendingMessage");

    if (status === IMPORT_STATUS.FINISHED)
      return translate("pages.userImportStatus.importFinishedMessage");

    return null;
  };

  return (
    <ImportStatusTemplate
      isLoading={!isLoaded}
      circularProgress={{
        progress,
      }}
      style={{ marginTop: 40 }}
      title={renderTitle()}
      subTitle={renderSubTitle()}
    >
      {isLoaded && renderContent()}
    </ImportStatusTemplate>
  );
};

export default ImportUsersStatus;
