import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getBroadcastRoomsCountHours } from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";

//* CUSTOM IMPORTS
import SliderLoader from "~/shared/components/molecules/cellLoaders/FieldsetLoader";
import { isListValid } from "~/shared/utils/validations";
import { getFullHoursBetweenDates, numberToTime } from "~/shared/utils/dates";
import CheckboxedBroadcastRooms from "~/modules/signed/modules/cms/components/lists/CheckboxedBroadcastRoomList";
import { Slider } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

import { SliderContainer, Marker, MarkerDescription, Checkbox } from "./styles";

function ProgrammingTab({
  broadcastRooms,
  broadcastRoomIds,
  setBroadcastRoomIds,
  allBroadcastRooms,
  setAllBroadcastRooms,
  setPercentWatch,
  percentWatch,
  showSchedule,
  setShowSchedule,
  onlyLive,
  setOnlyLive,
}) {
  //* STATES
  const [workloadSum, setWorkloadSum] = React.useState(0);
  const [size, setSize] = React.useState(broadcastRoomIds.length || 0);
  const [isCalculating, setIsCalculating] = React.useState(false);

  const { translate } = useTranslation();

  //* FUNCTIONS
  React.useEffect(() => {
    let dur = 0;

    if (isListValid(broadcastRooms)) {
      broadcastRooms.forEach(broadcastRoom => {
        const { open_live: openLive, end_live: endLive } = broadcastRoom;

        const { hours, minutes } = getFullHoursBetweenDates(openLive, endLive);

        const durationInMinutes = hours * 60 + minutes;

        dur += durationInMinutes;
      });
    }

    setWorkloadSum(dur);
  }, [broadcastRooms]);

  const handleSelectBroadcastRoom = (broadcastRoom, subtract = false) => {
    const { open_live: openLive, end_live: endLive } = broadcastRoom;

    const { hours, minutes } = getFullHoursBetweenDates(openLive, endLive);

    const durationInMinutes = hours * 60 + minutes;

    // Some or subtract broadcastRoom duration in minutes
    if (subtract) {
      setAllBroadcastRooms(false);
      setWorkloadSum(workloadSum - durationInMinutes);
      setSize(size - 1);
    } else {
      setWorkloadSum(workloadSum + Number(durationInMinutes));
      setSize(size + 1);
    }
  };

  function calculateTimeAvarage() {
    if (workloadSum > 0 && size >= 1) {
      return `${translate("common.meanOf")} ${numberToTime(
        workloadSum * (percentWatch / 100)
      )}`;
    }

    return `${translate("common.meanOf")} 0h`;
  }

  const unselectAll = () => {
    setWorkloadSum(0);
    setBroadcastRoomIds([]);
    setSize(0);
  };

  const selectAll = async () => {
    setIsCalculating(true);

    await getBroadcastRoomsCountHours().then(response => {
      const { total_workload: total, broadcast_room_ids } = response.data;

      const count = broadcast_room_ids.length;

      const workloadInMinutes = total * 60;

      setBroadcastRoomIds(broadcast_room_ids);
      setWorkloadSum(workloadInMinutes);
      setSize(count);
    });

    setIsCalculating(false);
  };

  const handleSelectAllCheckboxClick = () => {
    setAllBroadcastRooms(!allBroadcastRooms);

    //* If Select all broadcast rooms is already selected uncheck all checkboxes
    if (allBroadcastRooms) {
      unselectAll();
    } else {
      selectAll();
    }
  };

  return (
    <div
      style={{
        overflow: "hidden",
        paddingLeft: 12,
        paddingBottom: 40,
      }}
    >
      <Checkbox
        placeholder={translate(
          "entities.certificates.checkBoxEnable.placeholder"
        )}
        // placeholder="Habilitar página de programação no certificado"
        onClick={() => setShowSchedule(!showSchedule)}
        checked={showSchedule}
      />

      <Checkbox
        placeholder={translate(
          "entities.certificates.checkBoxRegard.placeholder"
        )}
        // placeholder="Considerar apenas programação ao vivo"
        onClick={() => setOnlyLive(!onlyLive)}
        checked={onlyLive}
      />

      <CheckboxedBroadcastRooms
        selectedBroadcastRooms={broadcastRoomIds}
        setSelectedBroadcastRooms={setBroadcastRoomIds}
        isAllSelected={allBroadcastRooms}
        onSelectAll={handleSelectAllCheckboxClick}
        onSelect={broadcastRoom => handleSelectBroadcastRoom(broadcastRoom)}
        onUnselect={broadcastRoom =>
          handleSelectBroadcastRoom(broadcastRoom, true)
        }
      />

      <SliderContainer>
        {isCalculating ? (
          <SliderLoader style={{ marginTop: 24 }} />
        ) : (
          <>
            <h5>{translate("entities.certificates.percentageMinimium")}</h5>

            <div>
              <Marker>{percentWatch}%</Marker>

              <MarkerDescription>{calculateTimeAvarage()}</MarkerDescription>
            </div>
          </>
        )}

        <Slider
          initialValue={percentWatch}
          onChange={value => setPercentWatch(value)}
          style={{ marginTop: 16 }}
        />
      </SliderContainer>
    </div>
  );
}

ProgrammingTab.propTypes = {
  broadcastRoomIds: PropTypes.arrayOf(PropTypes.object).isRequired,
  setBroadcastRoomIds: PropTypes.func.isRequired,
  allBroadcastRooms: PropTypes.bool.isRequired,
  setAllBroadcastRooms: PropTypes.func.isRequired,
  setPercentWatch: PropTypes.number.isRequired,
  percentWatch: PropTypes.number.isRequired,
  broadcastRooms: PropTypes.arrayOf(PropTypes.object).isRequired,
  showSchedule: PropTypes.bool.isRequired,
  setShowSchedule: PropTypes.func.isRequired,
  onlyLive: PropTypes.bool.isRequired,
  setOnlyLive: PropTypes.func.isRequired,
};

export default ProgrammingTab;
