import React from "react";
import PropTypes from "prop-types";
import { Form } from "../../molecules/Form";

//* CUSTOM IMPORTS
import { ColorSelect, Footer, ColorModal } from "./styles";
import { useTranslation } from "../../../hooks/translation";

//* FUNCTIONS
const colorPickerPropTypes = {
  /** Render the <b>Title</b> in the rigth top of the modal. */
  title: PropTypes.string,

  /** Render the <b>FooterButtons</b> in the rigth botton of the modal. */
  footerButtons: PropTypes.shape({
    entity: PropTypes.string,
    entityName: PropTypes.string,
    formId: PropTypes.string,
    onConfirm: PropTypes.func,
    onCancel: PropTypes.func,
    onClick: PropTypes.func,
    isLoading: PropTypes.bool,
    type: PropTypes.string,
    showModal: PropTypes.bool,
    modalType: PropTypes.oneOf(["create", "updtate", "delete"]),
    disableCancelButton: PropTypes.bool,
  }).isRequired,

  /** Handle the <b>ColorInput</b> form. */
  colorInput: PropTypes.shape({
    title: PropTypes.string,
    name: PropTypes.string,
    initialColor: PropTypes.string,
  }).isRequired,

  /** Handle the <b>Form</b> element */
  form: PropTypes.shape({
    onSubmit: PropTypes.func,
    onError: PropTypes.func,
    yupValidation: PropTypes.object,
  }).isRequired,

  /** Control the visibility of the modal */
  show: PropTypes.bool,

  /** Callback triggered when close button is cicked */
  onClose: PropTypes.func,
};

const colorPickerDefaultProps = {
  show: false,
  onClose: () => {},
  title: null,
};

/** ColorPicker is used to pick chroma key in questions preview */
function ColorPicker({
  show,
  onClose,
  footerButtons,
  form,
  colorInput,
  title,
  ...rest
}) {
  const { translate } = useTranslation();

  return (
    <ColorModal
      title={title || translate("components.colorPicker.title")}
      titleColor="#FD9F2B"
      show={show}
      onClose={onClose}
      {...rest}
    >
      <Form
        {...form}
        style={{
          minWidth: 300,
        }}
      >
        <ColorSelect title="Color" name="color" {...colorInput} />
        <Footer {...footerButtons} type="submit" />
      </Form>
    </ColorModal>
  );
}

ColorPicker.propTypes = colorPickerPropTypes;
ColorPicker.defaultProps = colorPickerDefaultProps;

export { ColorPicker, colorPickerPropTypes, colorPickerDefaultProps };
