import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

// APIs
import { listDropdownAttributes } from "~/modules/signed/modules/client/apis/cadasterItems/dropdownAttributes";

// CUSTOM IMPORTS
import { isListValid } from "../../../../../../../../../../shared/utils/validations";
import { Dropdown } from "../../../../../../../../../../shared/components/organisms/Dropdown";

function AttributesDropwdown({ refCode, initialData, ...rest }) {
  const { colors } = React.useContext(ThemeContext);

  // STATES
  const [attributes, setAttributes] = React.useState([]);
  const [isLoaded, setIsLoaded] = React.useState(false);

  // FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      await listDropdownAttributes({ attribute_ref: refCode }).then(
        response => {
          // Convert response to an objet that dropdown accepts
          setAttributes(
            response.data.map(attribute => {
              return { id: attribute.id, name: attribute.title };
            })
          );

          setIsLoaded(true);
        }
      );
    };

    if (!isListValid(initialData)) {
      list();
    } else {
      setIsLoaded(false);
      setAttributes(
        initialData.map(attribute => {
          return { id: attribute.id, name: attribute.title };
        })
      );

      setTimeout(() => setIsLoaded(true), 100);
    }
  }, [initialData, refCode]);

  const handleOnChange = () => {};

  return isLoaded ? (
    <Dropdown
      options={attributes}
      onChange={handleOnChange}
      withSearch
      {...rest}
    />
  ) : (
    <div
      style={{
        width: "100%",
        borderRadius: 8,
        height: 48,
        background: colors.contentLoaderBackground,
      }}
    />
  );
}

AttributesDropwdown.propTypes = {
  refCode: PropTypes.number.isRequired,
  initialData: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default AttributesDropwdown;
