import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { DialogModal, dialogModalPropTypes } from "../../molecules";
import { useTranslation } from "../../../hooks/translation";

const types = {
  CREATE: "create",
  UPDATE: "update",
  DELETE: "delete",
  CLONE: "clone",
};

const crudModalPropTypes = {
  /** If TRUE display modal */
  show: dialogModalPropTypes.show,

  /** Callback that is triggered when user click outside modal */
  onClose: dialogModalPropTypes.onClose,

  /** Callback that is triggered when user click into confirmButton */
  onConfirm: dialogModalPropTypes.onConfirm,

  /** Define the messages for the modal, based on this props */
  type: PropTypes.oneOf(["update", "create", "delete", "clone"]).isRequired,

  /** The name of the entity that will undergo the changes
   *
   * Example: An entity is <b>user</b>
   */
  entity: PropTypes.string.isRequired,

  /** The specific name of the entity that will undergo the changes
   *
   * Example: An entityName is <b>João</b>, that is the name of the <b>user</b>
   */
  entityName: PropTypes.string.isRequired,

  /** If TRUE display an spinner in confirmButton and hide de cancelButton */
  isLoading: dialogModalPropTypes.isLoading,
};

const crudModalDefaultProps = {
  show: false,
  onClose: () => {},
  onConfirm: () => {},
  isLoading: false,
};

/** CRUDModal implements the <b>DialogModal</b> component, it is commonly used
 * when you need an Modal that has all the CRUD options, like <b>delete</b> and
 * <b>edit</b>.
 */
function CRUDModal({
  show,
  type,
  entity,
  entityName,
  onClose,
  onConfirm,
  isLoading,
  ...rest
}) {
  // FUNCTIONS
  const { translate } = useTranslation();

  const handleOnClose = () => {
    onClose();
  };

  function renderTitle() {
    switch (type) {
      case types.CREATE:
        return `${translate("common.add")} ${entity}`;

      case types.UPDATE:
        return `${translate("common.edit")} ${entity}`;

      case types.DELETE:
        return `${translate("common.delete")} ${entity}`;

      case types.CLONE:
        return `Clonar ${entity}`;

      default:
    }

    return null;
  }

  function renderDescription() {
    switch (type) {
      case types.CREATE:
        return (
          <>
            {translate("common.AreYoSureYouWantToAdd")}{" "}
            <strong>{entityName}</strong>?
          </>
        );

      case types.UPDATE:
        return (
          <>
            {translate("common.AreYoSureYouWantToEdit")}{" "}
            <strong>{entityName}</strong>?
          </>
        );

      case types.DELETE:
        return (
          <>
            {translate("common.AreYouSureYouWantToDelete")}{" "}
            <strong>{entityName}</strong>?
          </>
        );

      case types.CLONE:
        return (
          <>
            Tem certeza que deseja clonar
            <strong>{entityName}</strong>?
          </>
        );

      default:
    }

    return null;
  }

  return (
    <DialogModal
      {...rest}
      show={show}
      onClose={handleOnClose}
      onCancel={handleOnClose}
      title={renderTitle()}
      description={renderDescription()}
      onConfirm={onConfirm}
      isLoading={isLoading}
      variant={type === "delete" && "error"}
      style={{ minWidth: 400 }}
    />
  );
}

CRUDModal.propTypes = crudModalPropTypes;

CRUDModal.defaultProps = crudModalDefaultProps;

export { CRUDModal, crudModalPropTypes, crudModalDefaultProps };
