import React from "react";
import * as Yup from "yup";

//* APIs
import {
  createVariable,
  getVariable,
} from "~/modules/signed/modules/cms/modules/admin/apis/variables";
import { checkEmailVerified } from "../../../../apis/emails";

//* CUSTOM IMPORTS
import { SenderEmailInput } from "../../../../../../components/SenderEmailInput";
import { Input, Spinner } from "~/shared/components/atoms";
import { Form } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useCMS } from "../../../../../../hooks";

const CHECK_STATUS = {
  IDLE: 1,
  VERIFIED: 2,
  NOT_VERIFIED: 3,
};

const EmailTab = () => {
  const { showFooterButtons, closeFooterButtons } = useCMS();

  //* STATE
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [initialData, setInitialData] = React.useState({});
  const [isLoading, setIsLoading] = React.useState(false);
  const [isVerified, setIsVerified] = React.useState(CHECK_STATUS.IDLE);

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    showFooterButtons({
      formId: "sender-email-form",
      disableCancelButton: true,
      isLoading,
    });

    return () => closeFooterButtons();
  }, [closeFooterButtons, isLoading, showFooterButtons]);

  React.useEffect(() => {
    const get = async () => {
      await getVariable({ key: "sender" }).then(response => {
        if (response.data.value)
          setInitialData(JSON.parse(response.data.value));

        setIsLoaded(true);
      });
    };

    get();
  }, []);

  const handleSubmit = async data => {
    setIsLoading(true);

    await checkEmailVerified({ email: data.email }).then(async response => {
      const { email } = response.data;

      setIsVerified(email ? CHECK_STATUS.VERIFIED : CHECK_STATUS.NOT_VERIFIED);

      if (email)
        await createVariable({
          key: "sender",
          value: { email: data.email, name: data.name },
        });
    });

    setIsLoading(false);
  };

  return isLoaded ? (
    <Form
      initialData={initialData}
      yupValidation={{
        email: Yup.string()
          .email(translate("handlers.email.isEmailInvalid"))
          .required(translate("handlers.email.isRequired")),
        name: Yup.string().required(translate("handlers.name.isRequired")),
      }}
      onSubmit={handleSubmit}
      id="sender-email-form"
      style={{ maxWidth: 400 }}
    >
      <SenderEmailInput
        variant={isVerified === CHECK_STATUS.NOT_VERIFIED && "error"}
        name="email"
        placeholder={translate(
          "components.input.emailSenderDefault.placeholder"
        )}
        title={translate("components.input.emailSenderDefault.title")}
      />

      <Input
        name="name"
        placeholder={translate(
          "components.input.emailSenderNameDefault.placeholder"
        )}
        title={translate("components.input.emailSenderNameDefault.title")}
        style={{ marginTop: 16 }}
      />
    </Form>
  ) : (
    <Spinner centered />
  );
};

export { EmailTab };
