import React from "react";
import { useLocation } from "react-router-dom";

//* CUSTOM IMPORTS
import { SwipeTabs } from "~/shared/components/molecules";
import { SettingsTab, ScoreTab, Ranking } from "./components";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminGamification } from "./hooks";
import { Spinner } from "~/shared/components/atoms";

function Gamification() {
  const { translate } = useTranslation();
  const { isLoaded } = useAdminGamification();
  const { state } = useLocation();

  //* STATES
  const [tabIndex, setTabIndex] = React.useState(0);

  //* FUNCTIONS
  React.useEffect(() => {
    if (state) if (state.tabIndex) setTabIndex(state.tabIndex);
  }, [state]);

  //* TABS
  const tabs = [
    {
      id: 1,
      name: translate("entities.gamification.cms.tab1"),
    },
    {
      id: 2,
      name: translate("entities.gamification.cms.tab2"),
    },
    {
      id: 3,
      name: translate("entities.gamification.cms.tab3"),
    },
  ];

  return isLoaded ? (
    <SwipeTabs
      onChange={value => {
        setTabIndex(value);
      }}
      index={tabIndex}
      controlled
      size="small"
      alignment="left"
      tabs={tabs}
      childrenStyle={{ padding: "30px 8px 0" }}
    >
      <SettingsTab />

      {tabIndex === 1 && <ScoreTab />}

      {tabIndex === 2 && <Ranking />}
    </SwipeTabs>
  ) : (
    <Spinner centered />
  );
}

export default Gamification;
