import React from "react";
import PropTypes from "prop-types";
import { MdSearch } from "react-icons/md";

//* CUSTOM IMPORTS
import { Typography, Button, Trans } from "../../atoms";
import { Box, Icon } from "./styles";

const emptyPropTypes = {
  /** Message can be an string or an element */
  message: PropTypes.oneOfType([PropTypes.string, PropTypes.element])
    .isRequired,

  /** An description of something user can do when component is empty */
  description: PropTypes.string,

  /** Display an icon above the message */
  icon: PropTypes.node,

  /** If button is not null show an button below message, default is null */
  button: PropTypes.shape({
    children: PropTypes.string,
    onClick: PropTypes.func,
  }),

  variant: PropTypes.oneOf(["default", "search"]),

  /** If <b>search</b> has data, render an bold word inside description */
  search: PropTypes.string,

  size: PropTypes.oneOf(["small", "large"]),
};

const emptyDefaultProps = {
  icon: null,
  description: null,
  button: null,
  size: "large",
  variant: "default",
  search: null,
};

/** Empty is commonlyu used to display an message when some component is empty */
function Empty({
  size,
  message,
  description,
  icon,
  button,
  variant,
  search,
  ...rest
}) {
  const renderMessageSize = () => {
    switch (size) {
      case "small":
        return null;

      case "large":
        return "tinyTitle";

      default:
        return null;
    }
  };

  const renderDescriptionSize = () => {
    switch (size) {
      case "small":
        return "regular";

      case "large":
        return "subTitle";

      default:
        return null;
    }
  };

  const renderIcon = () => {
    if (variant === "search") return <MdSearch />;

    return icon;
  };

  const renderMessage = () => {
    if (variant === "search")
      return (
        <Trans translationKey="components.empty.search.message">
          Nenhum resultado encontrado
        </Trans>
      );

    return message;
  };

  const renderDescription = () => {
    if (variant === "search")
      return (
        <Trans translationKey="components.empty.search.description">
          Não encontramos resultados relacionados a
        </Trans>
      );

    return description;
  };

  return (
    <Box {...rest}>
      {renderIcon() && <Icon>{renderIcon()}</Icon>}

      <Typography
        maxLines={2}
        className={`opaque ${renderMessageSize()} centered`}
      >
        {renderMessage()}
      </Typography>

      {renderDescription() && (
        <Typography
          style={{ marginTop: 10 }}
          maxLines={3}
          className={`smoked ${renderDescriptionSize()} centered`}
        >
          {renderDescription()}

          {search && <strong>{` "${search}" `}</strong>}
        </Typography>
      )}
      {button && (
        <Button size="small" style={{ marginTop: 30 }} {...button}>
          {button.children}
        </Button>
      )}
    </Box>
  );
}

Empty.propTypes = emptyPropTypes;

Empty.defaultProps = emptyDefaultProps;

export { Empty, emptyPropTypes, emptyDefaultProps };
