import React from "react";

// APIs
import { listPosters } from "~/modules/signed/modules/client/apis/posters";

// CUSTOM IMPORTS
import { isListValid } from "~/shared/utils/validations";
import { CellList } from "~/shared/components/molecules";
import { PosterCell } from "~/shared/components/organisms/cells";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { openPosterDetails } from "~/shared/routes/navigation";
import { Container, SearchBar, Title } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import { BookmarkButton } from "../../buttons/BookmarkButton";

function PosterList() {
  // STATES
  const [posters, setPosters] = React.useState({
    total: 0,
    perPage: 0,
    page: 0,
    lastPage: 0,
    data: [],
  });
  const [isSearching, setIsSearching] = React.useState(false);
  const [search, setSearch] = React.useState("");
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [hasNextPage, setHasNextPage] = React.useState(true);
  const [isPaging, setIsPaging] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const fetch = async () => {
      setIsSearching(true);

      await listPosters({ search }).then(response => {
        setPosters(response.data);
        setIsLoaded(true);
      });

      setHasNextPage(true);
      setIsSearching(false);
    };

    fetch();
  }, [search]);

  const onPagination = async () => {
    if (!hasNextPage) return;

    setIsPaging(true);

    await listPosters({ page: posters.page + 1, search }).then(response => {
      const { data, lastPage, page, perPage, total } = response.data;

      if (isListValid(response.data.data)) {
        setPosters({
          data: [...posters.data, ...data],
          lastPage,
          page,
          perPage,
          total,
        });
      }

      if (response.data.data.length !== perPage) setHasNextPage(false);
    });

    setIsPaging(false);
  };

  const renderCell = poster => {
    const {
      title,
      description,
      author,
      co_author: coAuthors,
      id,
      bookmark,
    } = poster;

    const coAuthorsSize = !coAuthors ? [] : String(coAuthors).split(",");

    return (
      <PosterCell
        customButton={
          <BookmarkButton
            isSelected={bookmark}
            apiType="posters"
            apiId={id}
            variant="icon"
          />
        }
        entity={translate("entities.poster.singular")}
        entityName={title}
        title={title}
        footerDescription={`${translate("common.per")} ${author}  ${
          coAuthorsSize.length > 0
            ? `${translate("common.andMore")} ${coAuthorsSize.length}`
            : ""
        }`}
        description={description}
        onClick={() => openPosterDetails(poster.id)}
      />
    );
  };

  return (
    <Container>
      <Title>
        Pôsteres <span>Acadêmicos</span>
      </Title>

      <SearchBar
        isSearching={isSearching}
        placeholder="Pesquise por autor, título ou assunto"
        onChange={value => setSearch(value)}
      />

      <CellList
        wrap
        isPaging={isPaging}
        isLoaded={isLoaded}
        data={posters.data}
        renderCell={renderCell}
        loaderListSize={9}
        renderCellLoader={() => (
          <CellLoader width={298} height={160} style={{ width: 298 }} />
        )}
        gap={10}
        withScroll
        onBottomReached={onPagination}
        scrollStyle={{ maxHeight: "calc(100vh - 80px)", padding: "10px" }}
      />
    </Container>
  );
}

export default PosterList;
