import React from "react";
import { useField } from "@unform/core";
import PropTypes from "prop-types";
import { MdKeyboardArrowDown } from "react-icons/md";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { Icon } from "../../atoms";
import { SearchBar } from "../../molecules";
import { isListValid } from "../../../utils/validations";
import {
  Container,
  DropdownContainer,
  Title,
  Options,
  Scroll,
  Option,
  InputArea,
  Input,
  IconButton,
  Error,
} from "./styles";

const dropdownPropTypes = {
  /** Array of options */
  options: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      value: PropTypes.string,
    })
  ).isRequired,

  className: PropTypes.string,

  /** Name is required to reference the dropdown value inside the form  */
  name: PropTypes.string.isRequired,

  /** The initial value to be displayed on the Dropdown */
  initialValue: PropTypes.any,

  /** Add an <b>SearchBar</b> inside the Dropdown options */
  withSearch: PropTypes.bool,

  /** Render an component at top of <b>Options</b> */
  optionHeaderComponent: PropTypes.element,

  /** Add an title above the Dropdown */
  title: PropTypes.string,

  /** Add an icon in the left */
  icon: PropTypes.element,

  /** If <b>TRUE</b> pin the placeholder even if user select an option */
  fixedPlaceholder: PropTypes.bool,

  /** Callback thats is triggered when user select an option, returns the option.value  */
  onChange: PropTypes.func,

  /** Callback thats is triggered when user click at the Dropdown   */
  onClick: PropTypes.func,

  style: PropTypes.object,
  /** Add an "*" at rigth of the title */
  isRequired: PropTypes.bool,
};

const dropdownDefaultProps = {
  onChange: () => {},
  onClick: () => {},
  className: null,
  initialValue: null,
  withSearch: false,
  optionHeaderComponent: null,
  title: null,
  icon: null,
  fixedPlaceholder: false,
  style: null,
  isRequired: false,
};

/** The Dropdown display an option list.
 *
 * The Dropdown component use <b>Form</b> component to anotate the selected value, so he
 *  needs to be inside an <b>Form</b>.
 *
 */

function Dropdown({
  className,
  options,
  title,
  name,
  isRequired,
  initialValue,
  onChange,
  onClick,
  icon,
  withSearch,
  optionHeaderComponent,
  fixedPlaceholder,
  style,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);
  const inputRef = React.useRef(null);
  const { fieldName, defaultValue, registerField, error } = useField(name);

  // STATES
  const [showOptions, setShowOptions] = React.useState(false);
  const [optionsState, setOptions] = React.useState([]);
  const [inputValue, setInputValue] = React.useState(initialValue);
  const isFilled = defaultValue || false;

  // FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, registerField]);

  React.useEffect(() => {
    setOptions(options);
  }, [options]);

  function handleOptionClick(previewValue, optionValue, option) {
    if (!fixedPlaceholder) setInputValue(previewValue);
    onChange(optionValue, option);
    setShowOptions(false);
  }

  function handleSearch(search) {
    const filteredArray = options.filter(option => {
      if (
        String(option.name)
          .toLowerCase()
          .includes(
            String(search)
              .toLowerCase()
              .trim()
          )
      )
        return option;

      return null;
    });

    setOptions(filteredArray);
  }

  const onDropdownClick = () => {
    onClick();
    setShowOptions(!showOptions);
  };

  return (
    <Container
      className={className}
      isFilled={isFilled}
      isOptionsOpened={showOptions}
      style={style}
    >
      {title && (
        <Title id="dropdown-title">
          {title}

          {isRequired && <span>*</span>}
        </Title>
      )}

      <DropdownContainer
        id="custom-drop-down"
        optionsOn={showOptions}
        isErrored={!!error}
      >
        {icon && (
          <Icon
            size={24}
            variant={isFilled || showOptions ? "accent" : "opaque"}
            iconName={icon}
            style={{ marginLeft: 16 }}
          />
        )}

        <InputArea>
          <Input
            {...rest}
            value={inputValue}
            type="text"
            disabled
            defaultValue={defaultValue}
            ref={inputRef}
          />
        </InputArea>

        <IconButton type="button" onClick={onDropdownClick}>
          <MdKeyboardArrowDown className="dropdown-icon" />
        </IconButton>

        {error && (
          <Error
            message={error}
            tipColor={colors.redWrong}
            icon={{ iconName: "error", variant: "error" }}
          />
        )}

        <Options show={showOptions}>
          {withSearch && (
            <SearchBar
              name={name}
              placeholder="Pesquisar"
              onChange={search => handleSearch(search)}
            />
          )}

          {optionHeaderComponent}

          <Scroll>
            {isListValid(optionsState) &&
              optionsState.map((option, index) => (
                <Option
                  variant="text"
                  key={option.id}
                  value={option.name}
                  onClick={() =>
                    handleOptionClick(option.name, option.value, option)
                  }
                  lastIndex={optionsState.length - 1 === index}
                >
                  {option.name}
                </Option>
              ))}
          </Scroll>
        </Options>
      </DropdownContainer>
    </Container>
  );
}

Dropdown.propTypes = dropdownPropTypes;

Dropdown.defaultProps = dropdownDefaultProps;

export { Dropdown, dropdownPropTypes, dropdownDefaultProps };
