import React from "react";
import PropTypes from "prop-types";
import { MdAddCircle } from "react-icons/md";

import { Button } from "./styles";

const outlinedButtonPropTypes = {
  /** Render an string inside the button */
  placeholder: PropTypes.string,

  /** Define the orientation of the button, default is "column" */
  direction: PropTypes.oneOf(["row", "column"]),

  justifyContent: PropTypes.oneOf(["flex-start", "center"]),

  /** If TRUE, Fill the background color with the "accent" color */
  isActive: PropTypes.bool,

  /** Define the icon of the button, default is an <b>MdAddCircle</b> icon */
  icon: PropTypes.element,
};

const outlinedDefaultProps = {
  placeholder: null,
  direction: "column",
  isActive: false,
  justifyContent: "center",
  icon: <MdAddCircle id="add-icon" />,
};

/** An Big Outlined Button */
function OutlinedButton({
  isActive,
  placeholder,
  direction,
  icon,
  justifyContent,
  ...rest
}) {
  return (
    <Button
      isActive={isActive}
      type="button"
      direction={direction}
      justifyContent={justifyContent}
      {...rest}
    >
      {icon}

      {placeholder && <h4>{placeholder}</h4>}
    </Button>
  );
}

OutlinedButton.propTypes = outlinedButtonPropTypes;

OutlinedButton.defaultProps = outlinedDefaultProps;

export { OutlinedButton, outlinedButtonPropTypes, outlinedDefaultProps };
