import React from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

//* ACTIONS
import {
  fetchSponsoredRooms,
  clearSponsoredRooms,
} from "~/modules/signed/modules/client/store/rooms/actions";

//* CUSTOM IMPORTS

import { isDataValid } from "~/shared/utils/validations";
import { Trans } from "~/shared/components/atoms";
import BroadcastRoomList from "~/modules/signed/modules/client/components/lists/BroadcastRoomList";

function SponsoredRoomList({ partnerId }) {
  //* REDUCER
  const dispatch = useDispatch();
  const sponsoredRoomsReducer = useSelector(
    state => state.rooms.sponsoredRooms
  );
  const { data: sponsoredRooms, isLoaded } = sponsoredRoomsReducer;

  //* FUNCTIONS

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(partnerId))
      dispatch(fetchSponsoredRooms(partnerId));

    return () => {
      isMounted = false;

      dispatch(clearSponsoredRooms());
    };
  }, [dispatch, partnerId]);

  return (
    sponsoredRooms.length > 0 && (
      <BroadcastRoomList
        title={
          <Trans translationKey="entities.marketRooms.sessionsOffered">
            Sessões ofertadas
          </Trans>
        }
        broadcastRooms={sponsoredRooms}
        isLoaded={isLoaded}
      />
    )
  );
}

SponsoredRoomList.propTypes = {
  partnerId: PropTypes.number.isRequired,
};

export default SponsoredRoomList;
