import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container } from "./styles";

const cardPropTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,

  /** If TRUE, disable the box shadow in CSS, default is "false" */
  disableBoxShadow: PropTypes.bool,
};

const cardDefaultProps = {
  disableBoxShadow: false,
};

/** The default Card of the application */
function Card({ children, disableBoxShadow, ...rest }) {
  return (
    <Container disableBoxShadow={disableBoxShadow} {...rest}>
      {children}
    </Container>
  );
}

Card.propTypes = cardPropTypes;
Card.defaultProps = cardDefaultProps;

export { Card, cardPropTypes, cardDefaultProps };
