export const userRoles = [
  {
    role: ADMIN.value,
    permissions: [
      {
        path: "/sponsor/*",
        action: "deny",
      },
      {
        path: "/*",
        action: "allow",
      },
    ],
  },
  {
    role: EDITOR.value,
    permissions: [
      {
        path: "/sponsor/*",
        action: "deny",
      },
      {
        path: "/admin/users/*",
        action: "deny",
      },
      {
        path: "/admin/certificates/*",
        action: "deny",
      },
      {
        path: "/admin/events/*",
        action: "deny",
      },
      {
        path: "/admin/emails/*",
        action: "deny",
      },
      {
        path: "/admin/settings/*",
        action: "deny",
      },
      {
        path: "/admin/dashboard/*",
        action: "deny",
      },
      {
        path: "/*",
        action: "allow",
      },
    ],
  },
  {
    role: SPONSOR.value,
    permissions: [
      {
        path: "/admin/*",
        action: "deny",
      },
      {
        path: "/broadcast-rooms/questions/*",
        action: "deny",
      },
      {
        path: "/*",
        action: "allow",
      },
    ],
  },
  {
    role: MODERATOR.value,
    permissions: [
      {
        path: "/admin/*",
        action: "deny",
      },
      {
        path: "/sponsor/*",
        action: "deny",
      },
      {
        path: "/*",
        action: "allow",
      },
    ],
  },
  {
    role: COMMOM_USER.value,
    permissions: [
      {
        path: "/admin/*",
        action: "deny",
      },
      {
        path: "/broadcast-rooms/questions/*",
        action: "deny",
      },
      {
        path: "/sponsor/*",
        action: "deny",
      },
      {
        path: "/*",
        action: "allow",
      },
    ],
  },
];

export const isRouteAllowed = (accessType, path) => {
  const userRole = userRoles.find(item => {
    return item.role === accessType;
  });

  if (!userRole) return false;

  const findedPermission = userRole.permissions.find(permission => {
    const searchKey = permission.path.replace("/*", "[a-z0-9/_]*");

    return path.match(searchKey);
  });

  if (!findedPermission) return false;

  if (findedPermission.action === "deny") return false;

  return true;
};
