import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { MdPeople, MdAddCircle } from "react-icons/md";

// ACTIONS
import {
  listNetworkRooms,
  deleteNetworkRoom,
} from "~/modules/signed/modules/cms/modules/admin/store/networkRooms/actions";

// CUSTOM IMPORTS
import {
  openCreateNetworkRoom,
  openEditNetworkRoom,
} from "~/shared/routes/navigation";
import { OutlinedButton } from "~/shared/components/atoms";
import { NetworkRoomCell } from "~/shared/components/organisms/cells";
import { CellList, Empty } from "~/shared/components/molecules";
import CellLoader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { useTranslation } from "~/shared/hooks/translation";

function NetworkRoomList() {
  const { translate } = useTranslation();
  const {
    dTranslate,
    dTranslateTags,
    dTranslatePartners,
  } = useDynamicTranslation();

  // REDUCER
  const dispatch = useDispatch();
  const { data: networkRooms, isLoaded, isDeleting } = useSelector(
    state => state.adminNetworkRooms.list
  );
  const [cellId, setCellId] = React.useState(null);

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted) dispatch(listNetworkRooms());

    return () => {
      isMounted = false;
    };
  }, [dispatch]);

  const renderCell = networkRoom => {
    const { room, id } = networkRoom;

    return (
      <NetworkRoomCell
        key={id}
        bookmark={room.bookmark}
        title={dTranslate(room.title)}
        image={room.logo}
        description={dTranslate(room.description)}
        tags={dTranslateTags(room.tags)}
        isAdmin
        bottomTags={dTranslatePartners(room.partners)}
        crudOptionsModal={{
          onDelete: () => {
            setCellId(id);
            dispatch(deleteNetworkRoom(id));
          },
          onEdit: () => openEditNetworkRoom(networkRoom.id),
          entity: translate("entities.networkRooms.crudModal"),
          entityName: dTranslate(room.title),
        }}
        onClick={() => openEditNetworkRoom(id)}
      />
    );
  };

  const isEmpty = isLoaded && networkRooms.length === 0;

  return (
    <CellList
      isDeleting={isDeleting === "PENDING"}
      cellId={cellId}
      isLoaded={isLoaded}
      data={networkRooms}
      renderCell={renderCell}
      renderCellLoader={() => <CellLoader />}
      firstCell={
        !isEmpty && (
          <OutlinedButton
            type="button"
            onClick={openCreateNetworkRoom}
            placeholder={translate(
              "entities.networkRooms.outlineButtonPlaceholder"
            )}
            style={{ width: 300 }}
          />
        )
      }
      loaderListSize={9}
      containerStyle={{ height: "100%" }}
      listStyle={{ height: isEmpty ? "100%" : "unset" }}
      emptyComponent={
        <Empty
          style={{ position: "relative", bottom: 100 }}
          icon={<MdPeople />}
          message={translate("entities.networkRooms.empty.message")}
          description={translate("entities.networkRooms.empty.description")}
          button={{
            onClick: openCreateNetworkRoom,
            children: translate("entities.networkRooms.add"),
            icon: <MdAddCircle />,
          }}
        />
      }
      gap={20}
    />
  );
}

export default NetworkRoomList;
