import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Card, Icon, Content } from "./styles";
import { iconPropTypes, Typography, typographyPropTypes } from "../../atoms";

const iconedCardPropTypes = {
  /** Control the card color schema, default is <b>accent</b> */
  color: PropTypes.oneOf(["surface", "accent", "primary"]),
  /** Icon to be rendered, to more details check <b>Icon</b> */
  icon: PropTypes.shape(iconPropTypes),
  /** Text to be rendered, to more details check <b>Typography</b> */
  description: PropTypes.shape(typographyPropTypes),
};

const iconedCardPropTypesDefaultProps = {
  color: "surface",
  icon: null,
  description: null,
};

/** Card that show a icon and/or a text */
function IconedCard({ color, icon, description, ...rest }) {
  return (
    <Card {...rest} color={color}>
      <Content>
        <Icon {...icon} />
        <Typography {...description} />
      </Content>
    </Card>
  );
}

IconedCard.propTypes = iconedCardPropTypes;
IconedCard.defaultProps = iconedCardPropTypesDefaultProps;

export { IconedCard, iconedCardPropTypes, iconedCardPropTypesDefaultProps };
