import { getFullHours, getTimeCourse, TIME_COURSE } from "~/shared/utils/dates";

/** This constant store all availableHours divided into time course based on availableDates */
export const getAvailableTimeCourser = (availableHours, translate) => {
  if (!availableHours.length) return [];

  const morning = [];
  const evening = [];
  const night = [];
  const dawn = [];

  availableHours.forEach((hour, index) => {
    const { startDate, endDate, isBlocked } = hour;
    const { EVENING, MORNING, NIGHT } = TIME_COURSE;

    const hourRange = {
      id: index,
      name: `${getFullHours(startDate)} ${translate(
        "common.at"
      )} ${getFullHours(endDate)}`,
      hour,
      isBlocked,
    };

    /** Check the startDate timeCourse then add into it respective array. */
    switch (getTimeCourse(startDate)) {
      case MORNING:
        morning.push(hourRange);
        break;

      case EVENING:
        evening.push(hourRange);
        break;

      case NIGHT:
        night.push(hourRange);
        break;

      default:
        dawn.push(hourRange);
        break;
    }
  });

  const availableTimeCourse = [];

  if (dawn.length)
    availableTimeCourse.push({
      id: 4,
      title: translate("common.dawn"),
      data: dawn,
    });

  if (morning.length)
    availableTimeCourse.push({
      id: 1,
      title: translate("common.morning"),
      data: morning,
    });

  if (evening.length)
    availableTimeCourse.push({
      id: 2,
      title: translate("common.evening"),
      data: evening,
    });

  if (night.length)
    availableTimeCourse.push({
      id: 3,
      title: translate("common.night"),
      data: night,
    });

  return availableTimeCourse;
};
