import React from "react";

// CUSTOM IMPORTS
import NetworkRoomForm from "~/modules/signed/modules/cms/modules/admin/pages/NetworkRooms/pages/components/NetworkRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminNetworkRooms } from "~/shared/routes/navigation";

import { useTranslation } from "~/shared/hooks/translation";

function CreateNetworkRoom() {
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.networkRooms.plural"),
        function: openAdminNetworkRooms,
      },
      {
        id: 2,
        name: translate("entities.networkRooms.newNetworkingRoom"),
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return <NetworkRoomForm />;
}

export default CreateNetworkRoom;
