import produce from "immer";

//* CUSTOM IMPORTS
import types from "./types";

const INITIAL_STATE = {
  categories: {
    data: [],
    isLoaded: false,
  },
};

export default function roomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case types.CATEGORIES.RECEIVE: {
        const { marketRoomsCategories } = action.payload;

        draft.categories.data = marketRoomsCategories;

        draft.categories.isLoaded = true;

        break;
      }

      //* SIGN OUT
      case "@auth/SIGN_OUT":
      case types.CATEGORIES.CLEAR: {
        draft.categories.data = [];
        draft.categories.isLoaded = false;

        break;
      }
      default:
    }
  });
}
