import styled, { keyframes } from "styled-components";

// CUSTOM IMPORTS
import { Card as CardC } from "../../atoms/Card/index";
import { VideoPlayer } from "../../molecules";

const showAnimation = keyframes`
  from{
    opacity: 0;
    bottom: -300px;
  }

  to{
    bottom: 16px;
    opacity: 1;
  }
`;

const closeAnimation = keyframes`
  from{
    bottom: 16px;
    opacity: 1;
  }

  to{
    opacity: 0;
    bottom: -300px;
  }
`;

export const Card = styled(CardC)`
  max-width: 400px;
  position: fixed;
  bottom: 16px;
  right: 32px;
  display: flex;
  flex-direction: column;
  width: 29.28vw;
  padding: 0;
  animation: ${props => (props.isClosing ? closeAnimation : showAnimation)} 0.5s
    ease;
  min-width: 350px;
  min-height: 262px;

  #pip-inner-card {
    position: relative;
  }

  .fuXbue {
    height: 224px;
  }
`;

export const Header = styled.div`
  display: flex;
  width: 100%;
  justify-content: flex-end;
  padding: 8px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
`;

export const PlayerWrapper = styled.div`
  width: 100%;
  cursor: pointer;
  position: relative;

  #video-player-iframe {
    width: 100%;
    height: 224px;
  }

  &:hover {
    .pip-button {
      opacity: 1;
    }
  }
`;

export const PipButton = styled.button`
  opacity: 0;
  transition: opacity 0.3s;
  margin-left: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  background: ${props => props.theme.colors.surface};
  box-shadow: 0 2px 6px ${props => props.theme.colors.shadow};
  padding: 7px;
  border-radius: 50%;

  svg {
    height: 18px;
    width: 18px;
    color: ${props => props.theme.colors.opaque};
  }

  &:hover {
    svg {
      color: ${props => props.theme.colors.accent};
    }
  }
`;

export const Content = styled.div`
  padding: 16px;
`;

export const Player = styled(VideoPlayer)`
  position: relative;
  width: 100%;
  box-shadow: none;
  max-width: unset;
  border-radius: 8px 8px 0 0;

  .plyr--video {
    border-radius: 8px 8px 0 0;
  }
`;
