import produce from "immer";

//* CUSTOM IMPORTS
import { updateItem } from "~/shared/utils/lists";
import { isDataValid } from "~/shared/utils/validations";
import chatTypes from "./types";

const INITIAL_STATE = {
  messages: {
    pinnedMessage: null,
    data: [],
    // Store is a array that only used when user has options open
    store: [],
    blockedUsers: [],
    isLoaded: false,
    isOptionsOpen: false,
    messageId: null,
    isDeleting: false,
    isSending: false,
  },
};

export default function assetsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case chatTypes.channel.RECEIVE_MESSAGES: {
        const { messages } = action.payload;

        if (isDataValid(messages)) {
          draft.messages.data = messages.reverse();
        }

        draft.messages.isLoaded = true;

        break;
      }

      case chatTypes.channel.admin.DELETE_MESSAGE: {
        const { messageId } = action.payload;

        draft.messages.messageId = messageId;
        draft.messages.isDeleting = true;

        break;
      }

      case chatTypes.channel.admin.DELETE_MESSAGE_SUCCESSFUL:
      case chatTypes.channel.admin.DELETE_MESSAGE_FAILED: {
        draft.messages.messageId = null;
        draft.messages.isDeleting = false;

        break;
      }

      case chatTypes.channel.RECEIVE_SINGLE_MESSAGE: {
        const { message } = action.payload;

        if (isDataValid(message)) {
          //* If user has options open, store all single messages inside store to
          //* do not change list state position
          if (draft.messages.isOptionsOpen) {
            draft.messages.store = [...draft.messages.store, message];
          } else {
            const auxList = draft.messages.data;

            //* * Remove message from memory, if list has more than 100 messages */
            if (auxList.length > 100) {
              auxList.shift();

              draft.messages.data = [...auxList, message];
            } else {
              draft.messages.data = [...draft.messages.data, message];
            }
          }
        }

        draft.messages.isSending = false;

        break;
      }

      case chatTypes.channel.RECEIVE_PINNED_MESSAGE: {
        const { pinnedMessage } = action.payload;

        draft.messages.pinnedMessage = pinnedMessage;

        break;
      }

      case chatTypes.channel.RECEIVE_SINGLE_MESSAGE_LIKES: {
        const { message } = action.payload;

        draft.messages.data = updateItem({
          list: draft.messages.data,
          updatedItem: message,
        });

        break;
      }

      case chatTypes.channel.SEND_MESSAGE: {
        //* Now Socket need to wait server to receive the message created

        draft.messages.isSending = true;

        break;
      }

      // RECEIVE BLOCKED USERS
      case chatTypes.channel.RECEIVE_BLOCKED_USERS: {
        const { blockedUsers } = action.payload;

        if (isDataValid(blockedUsers))
          draft.messages.blockedUsers = blockedUsers;

        break;
      }

      case chatTypes.options.OPEN: {
        draft.messages.isOptionsOpen = true;

        break;
      }

      case chatTypes.options.CLOSE: {
        draft.messages.isOptionsOpen = false;

        //* *Add all data inside store and then clear store */
        draft.messages.data = [...draft.messages.data, ...draft.messages.store];
        draft.messages.store = [];

        break;
      }

      case chatTypes.CLEAR: {
        draft.messages.data = [];
        draft.messages.store = [];
        draft.messages.blockedUsers = [];
        draft.messages.isOptionsOpen = false;
        draft.messages.isLoaded = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.messages.data = [];
        draft.messages.isLoaded = false;
        draft.messages.isOptionsOpen = false;
        draft.messages.blockedUsers = [];
        draft.messages.store = [];
        draft.messages.isSending = false;
        draft.messages.messageId = null;
        draft.messages.isDeleting = false;
        draft.messages.pinnedMessage = null;

        break;
      }
      default:
    }
  });
}
