import styled from "styled-components";

export const Container = styled.div`
  font-size: 14px;
  font-family: "Montserrat", sans-serif;

  color: ${props => {
    const { theme, limit, value } = props;

    if (value > limit) return theme.colors.redWrong;

    if (value <= limit && value >= limit * 0.7)
      return theme.colors.yellowWarning;

    return theme.colors.normal;
  }};
`;
