import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Avatar,
  Typography,
  Box,
  typographyPropTypes,
  avatarPropTypes,
} from "../../atoms";

const infosPropTypes = {
  /** Control all props for <b>Avatar</b> component. See all props into <b>Atoms/Avatar</b> */
  avatar: PropTypes.shape(avatarPropTypes),

  title: PropTypes.string.isRequired,

  /** Control the title styles, use className to pass all styles.
   * See all classNames in <b>Atoms/Typography</b>
   */
  titleStyles: typographyPropTypes.className,

  subTitle: PropTypes.string,

  /** Control the title styles, use className to pass all styles.
   * See all classNames in <b>Atoms/Typography</b>
   */
  subTitleStyles: typographyPropTypes.className,
};

const infosDefaultProps = {
  avatar: null,

  subTitle: null,

  titleStyles: "bold regular",

  subTitleStyles: "opaque small",
};

const Infos = ({
  avatar,
  title,
  titleStyles,
  subTitle,
  subTitleStyles,
  ...rest
}) => {
  return (
    <Box display="flex" alignItems="center" {...rest}>
      {avatar && <Avatar {...avatar} />}

      <Box marginLeft="12px">
        <Typography
          className={titleStyles}
          maxLines={1}
          style={{ maxWidth: "160px" }}
        >
          {title}
        </Typography>

        {subTitle && (
          <Typography className={subTitleStyles}>{subTitle}</Typography>
        )}
      </Box>
    </Box>
  );
};

Infos.propTypes = infosPropTypes;

Infos.defaultProps = infosDefaultProps;

export { Infos, infosPropTypes, infosDefaultProps };
