import types from "./types";

// BROADCAST ROOM
export function fetchTransmissionRoom(roomId) {
  return {
    type: "@rooms/FETCH_TRANSMISSION_ROOM",
    payload: { roomId },
  };
}
export function fetchTransmissionRoomSuccessful(transmissionRoom) {
  return {
    type: "@rooms/FETCH_TRANSMISSION_ROOM_SUCCESSFUL",
    payload: { transmissionRoom },
  };
}
export function clearBroadcastRoom() {
  return {
    type: types.BROADCAST_ROOM.CLEAR,
  };
}

export function setIsBroadcastRoomPrivate() {
  return {
    type: "@rooms/SET_BROADCAST_ROOM_PRIVATE",
  };
}

// FETCH SPONSORED ROOMS
export function fetchSponsoredRooms(partnerId) {
  return {
    type: "@rooms/FETCH_SPONSORED_ROOMS",
    payload: { partnerId },
  };
}
export function fetchSponsoredRoomsSuccessful(sponsoredRooms) {
  return {
    type: "@rooms/FETCH_SPONSORED_ROOMS_SUCCESSFUL",
    payload: { sponsoredRooms },
  };
}
export function clearSponsoredRooms() {
  return {
    type: "@rooms/CLEAR_SPONSORED_ROOMS",
  };
}

// SEND QUESTION
export function sendRoomQuestion(roomId, question) {
  return {
    type: "@rooms/SEND_ROOM_QUESTION",
    payload: { roomId, question },
  };
}

export function sendRoomQuestionSuccessful() {
  return {
    type: "@rooms/SEND_ROOM_QUESTION_SUCCESSFUL",
  };
}

// GET TRANSMISSION ROOM RATE
export function fetchTransmissionRoomRate(roomId) {
  return {
    type: "@rooms/FETCH_TRANSMISSION_ROOM_RATE",
    payload: { roomId },
  };
}

export function fetchTransmissionRoomRateSuccessful(roomRate) {
  return {
    type: "@rooms/FETCH_TRANSMISSION_ROOM_RATE_SUCCESSFUL",
    payload: { roomRate },
  };
}

// EVALUATE TRANSMISSION ROOM
export function evaluateTransmissionRoom(roomId, rate, description = "") {
  return {
    type: "@rooms/EVALUATE_TRANSMISSION_ROOM",
    payload: { roomId, rate, description },
  };
}

export function clearRoomQuestion() {
  return {
    type: "@rooms/CLEAR_ROOM_QUESTION",
  };
}

// ACTIVE TRANSMISSION ROOM THEATER MODE
export function activeTheaterMode() {
  return {
    type: "@rooms/ACTIVE_THEATER_MODE",
  };
}

export function deactiveTheaterMode() {
  return {
    type: "@rooms/DEACTIVE_THEATER_MODE",
  };
}
