import React from "react";
import PropTypes from "prop-types";

// APIs
import { listDropdownAttributes } from "~/modules/signed/modules/client/apis/cadasterItems/dropdownAttributes";

// CUSTOM IMPORTS
import { TagGroup } from "~/shared/components/molecules";

const userTagsPropTypes = {
  selectedTags: PropTypes.arrayOf(PropTypes.number).isRequired,
  refCode: PropTypes.number.isRequired,
};

function UserTags({ selectedTags, refCode }) {
  // STATES
  const [tags, setTags] = React.useState([]);

  // FUNCTIONS
  React.useEffect(() => {
    listDropdownAttributes({
      attribute_ref: refCode,
    }).then(response => {
      setTags(response.data);
    });
  }, [refCode]);

  const renderTags = () => tags.filter(tag => selectedTags.includes(tag.id));

  return (
    <TagGroup
      isLoading={false}
      wrap
      size="small"
      variant="outlined"
      borderRadius={4}
      initialData={null}
      tags={renderTags().map(tag => ({
        id: tag.id,
        name: tag.title,
        isSelected: true,
        onClick: () => {},
      }))}
      style={{ marginBottom: "24px" }}
    />
  );
}

UserTags.propTypes = userTagsPropTypes;

export { UserTags };
