import styled from "styled-components";
import PerfectScroll from "react-perfect-scrollbar";

//* CUSTOM IMPORTS
import { LoaderContainer } from "~/shared/components/molecules";

export const Scroll = styled(PerfectScroll)`
  max-height: calc(100vh - 265px);
  padding-top: 24px;
`;

export const Container = styled.div``;

export const ContentLoader = styled(LoaderContainer)`
  padding-top: 24px;

  .loader-cell {
    margin-bottom: 40px;
  }
`;

export const EmptyContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 80px;

  svg {
    height: 100px;
    width: 100px;
    color: ${props => props.theme.colors.smoked};
  }

  h4 {
    margin-top: 16px;
    color: ${props => props.theme.colors.smoked};
  }
`;
