import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const stickyNotePropTypes = {
  /** Define the bgColor for StickyNotes in <b>hexadecimal</b>.
   * Default color is <b>#FFACDB</b>
   */
  color: PropTypes.string,

  /** Define the size in <b>px</b> for StickNote Container.
   * Default size is <b>200</b>
   */
  size: PropTypes.number,

  children: PropTypes.element.isRequired,

  /** Control the <b>Fold</b> component position in <b>StickyNote</b>.
   * default is <b>bottomLeft</b>.
   */
  foldPosition: PropTypes.oneOf([
    "topRight",
    "topLeft",
    "bottomRight",
    "bottomLeft",
  ]),

  /** Control the color of the fold background color */
  foldColor: PropTypes.string,
};

const stickyNoteDefaultProps = {
  color: "#FFACDB",
  size: 200,
  foldPosition: "bottomLeft",
  foldColor: null,
};

const StickyNote = ({
  color,
  size,
  children,
  foldPosition,
  foldColor,
  ...rest
}) => {
  return (
    <Container
      color={color}
      size={size}
      foldPosition={foldPosition}
      foldColor={foldColor}
      {...rest}
    >
      {children}
    </Container>
  );
};

StickyNote.propTypes = stickyNotePropTypes;

StickyNote.defaultProps = stickyNoteDefaultProps;

export { StickyNote, stickyNotePropTypes, stickyNoteDefaultProps };
