import React from "react";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listAdminRegistrationFields,
  clearAdminRegistrationFields,
  setRegistrationFields,
  deleteRegistrationField,
} from "~/modules/signed/modules/cms/modules/admin/store/registrationFields/actions";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import CellLoader from "../../../../../../../shared/components/molecules/cellLoaders/InputLoader";
import { DraggableList, DialogModal } from "~/shared/components/molecules";
import { RegistrationFieldCell } from "~/modules/signed/modules/cms/components/cells/RegistrationFieldCell";
import { openRegistrationFieldModal } from "~/shared/routes/navigation";
import { ContentLoader } from "./styles";

function RegistrationFieldList() {
  const dispatch = useDispatch();
  const { data: registrationFields, isLoaded, isDeleting } = useSelector(
    state => state.adminRegistrationFields.list
  );

  const { translate } = useTranslation();

  //* STATES
  const [registrationField, setRegistrationField] = React.useState(null);
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();

  React.useEffect(() => {
    dispatch(listAdminRegistrationFields());

    return () => {
      dispatch(clearAdminRegistrationFields());
    };
  }, [dispatch]);

  function handleSortList(sortedList) {
    dispatch(setRegistrationFields({ registrationFields: sortedList }));
  }

  const handleDeleteField = () => {
    dispatch(
      deleteRegistrationField({
        registrationFieldId: registrationField.id,
      })
    );
  };

  React.useMemo(() => {
    if (isDeleting === "FINISH") {
      setShowDeleteModal(false);
      setRegistrationField(null);
    }
  }, [isDeleting]);

  return isLoaded ? (
    <>
      <DraggableList
        data={registrationFields}
        onDragFinish={handleSortList}
        renderCell={(field, index) => {
          const {
            placeholder,
            title,
            required,
            deletable,
            dropdown_attributes,
            icon_type: iconType,
          } = field;

          return (
            <RegistrationFieldCell
              index={100 - index}
              title={dTranslate(title)}
              isRequired={required}
              dynamicField={{
                inputType: field.type,
                icon: iconType,
                placeholder: dTranslate(placeholder),
                name: field.ref_code,
                refCode: field.ref_code,
                dropdownOptions: dropdown_attributes,
              }}
              onDelete={
                deletable
                  ? () => {
                      setShowDeleteModal(true);
                      setRegistrationField(field);
                    }
                  : null
              }
              onEdit={() => openRegistrationFieldModal(field, true)}
            />
          );
        }}
      />

      {showDeleteModal && registrationField && (
        <DialogModal
          show={showDeleteModal}
          title={translate("entities.registrationFields.delete")}
          description={`${translate(
            "entities.registrationFields.deleteMessage"
          )} ${dTranslate(registrationField.title)}?`}
          onClose={() => setShowDeleteModal(false)}
          onCancel={() => setShowDeleteModal(false)}
          onConfirm={handleDeleteField}
          isLoading={isDeleting === "PENDING"}
        />
      )}
    </>
  ) : (
    <ContentLoader cellLoader={<CellLoader />} size={4} />
  );
}

export default RegistrationFieldList;
