import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import { createDate } from "~/shared/utils/dates";
import scheduleTypes from "./types";
import {
  listBroadcastRoomScheduleSuccessful,
  createScheduleFailed,
  createScheduleSuccessful,
  updateScheduleFailed,
  updateScheduleSuccessful,
  deleteScheduleFailed,
  deleteScheduleSuccessful,
  clearForm,
} from "./actions";
import {
  setScheduleIds,
  addScheduleId,
  removeScheduleId,
} from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";
import { isListValid } from "~/shared/utils/validations";

export function* listBroadcastRoomSchedule({ payload }) {
  try {
    const { broadcastRoomId } = payload;

    const response = yield call(
      api.get,
      `admin/schedule?broadcastRoomId=${broadcastRoomId}`
    );

    if (validResponseStatus(response.status)) {
      const schedules = response.data;

      yield put(listBroadcastRoomScheduleSuccessful(schedules));

      // Fill scheduleIds BroadcastRoom FORM list
      if (isListValid(schedules)) {
        const scheduleIds = schedules.map(schedule => schedule.id);

        yield put(setScheduleIds(scheduleIds));
      }
    }
  } catch (err) {
    console.log(err);
  }
}

export function* create({ payload }) {
  try {
    const { schedule, broadcastRoomId } = payload;

    const {
      title,
      description,
      startTime,
      endTime,
      date,
      speakersIds,
    } = schedule;

    const parsedStartTime = createDate({ date, time: startTime });
    const parsedEndTime = createDate({ date, time: endTime });

    const formattedSchedule = {
      id: 1,
      title,
      description,
      start_time: parsedStartTime,
      end_time: parsedEndTime,
      broadcast_room_id: broadcastRoomId,
      speakersIds,
    };

    const response = yield call(api.post, `admin/schedule`, formattedSchedule);

    if (validResponseStatus(response.status)) {
      const newSchedule = response.data;

      yield put(createScheduleSuccessful(newSchedule));

      yield put(addScheduleId(newSchedule.id));
    }
    yield put(clearForm());
  } catch (err) {
    console.log(err);
    yield put(createScheduleFailed());
  }
}

export function* update({ payload }) {
  try {
    const { schedule, broadcastRoomId, scheduleId } = payload;

    const {
      title,
      description,
      startTime,
      endTime,
      date,
      speakersIds,
    } = schedule;

    const parsedStartTime = createDate({ date, time: startTime });
    const parsedEndTime = createDate({ date, time: endTime });

    const formattedSchedule = {
      title,
      description,
      start_time: parsedStartTime,
      end_time: parsedEndTime,
      broadcast_room_id: broadcastRoomId,
      speakersIds,
    };

    const response = yield call(
      api.put,
      `admin/schedule/${scheduleId}`,
      formattedSchedule
    );

    if (validResponseStatus(response.status)) {
      const newSchedule = response.data;

      yield put(updateScheduleSuccessful(newSchedule));

      yield put(addScheduleId(newSchedule.id));
    }
    yield put(clearForm());
  } catch (err) {
    console.log(err);
    yield put(updateScheduleFailed());
  }
}

export function* destroy({ payload }) {
  try {
    const { scheduleId } = payload;

    const response = yield call(api.delete, `admin/schedule/${scheduleId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteScheduleSuccessful(scheduleId));
      yield put(removeScheduleId(scheduleId));
    }
    yield put(clearForm());
  } catch (err) {
    console.log(err);
    yield put(deleteScheduleFailed());
  }
}

export default all([
  takeLatest(
    "@schedules/LIST_BROADCAST_ROOM_SCHEDULE",
    listBroadcastRoomSchedule
  ),
  takeLatest(scheduleTypes.CREATE.REQUEST, create),
  takeLatest(scheduleTypes.UPDATE.REQUEST, update),
  takeLatest(scheduleTypes.DELETE.REQUEST, destroy),
]);
