import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  createRateSuccessful,
  createRateFailed,
  showRateSuccessful,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// CREATE
export function* create({ payload }) {
  try {
    const { rateData } = payload;

    const response = yield call(api.post, "/rateroom", rateData);

    if (validResponseStatus(response.status)) {
      yield put(createRateSuccessful());
    }
  } catch (err) {
    yield put(createRateFailed());
    console.log(err);
  }
}

// SHOW
export function* show({ payload }) {
  try {
    const { roomId, refId: ref_id, type } = payload;

    const response = yield call(api.get, `/rateroom/${roomId}`, {
      params: {
        ref_id,
        type,
      },
    });

    if (validResponseStatus(response.status)) {
      const { rate } = response.data;

      yield put(showRateSuccessful(rate));
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.SHOW.REQUEST, show),
]);
