import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  createNotification,
  updateNotification,
} from "~/modules/signed/modules/cms/modules/admin/store/notifications/actions";

//*  CUSTOM IMPORTS
import { getFullHours, getFullDate, createDate } from "~/shared/utils/dates";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import i18n from "~/shared/translation/i18n";
import { useTranslation } from "~/shared/hooks/translation";
import { Form, SwipeTabs } from "~/shared/components/molecules";
import { CRUDModal } from "~/shared/components/organisms";
import PersonalizeRepeatModal from "./components/PersonalizeModal";

//* TABs
import InformationTab from "./components/InformationTab";
import PerformanceTab from "./components/PerformanceTab";
import SegmentationTab from "./components/SegmentationTab";

function NotificationForm({ notification, editMode, onClose, ...rest }) {
  const { translate } = useTranslation();
  const { showFooterButtons, closeFooterButtons } = useCMS();

  //* REDUCERS
  const dispatch = useDispatch();
  const { isCreating, isUpdating } = useSelector(
    state => state.adminNotifications.list
  );

  //* STATES
  const [tabIndex, setTabIndex] = React.useState(0);
  const [formData, setFormData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);
  const [selectedGroups, setSelectedGroups] = React.useState([]);
  const [showModal, setShowModal] = React.useState(false);
  const [sendNow, setSendNow] = React.useState(false);
  const [formInitialData, setFormInitialData] = React.useState({});
  const [repeatValue, setRepeatValue] = React.useState("");
  const [startDate, setStartDate] = React.useState(null);
  const [endDate, setEndDate] = React.useState(null);
  const [showModalPersonalize, setShowModalPersonalize] = React.useState(false);

  //* TABS
  const tabs = [
    {
      id: 1,
      name: i18n.t("common.performance"),
      hidden: !editMode,
    },
    {
      id: 2,
      name: i18n.t("common.information"),
      error: infoTabError,
    },
    { id: 3, name: i18n.t("common.segmentation") },
  ];

  //* FUNCTIONS
  React.useEffect(() => {
    showFooterButtons({
      formId: "notification-form",
      onCancel: onClose,
    });

    return () => {
      closeFooterButtons();
    };
  }, [closeFooterButtons, onClose, showFooterButtons]);

  React.useEffect(() => {
    if (repeatValue === "personalize") setShowModalPersonalize(true);
  }, [repeatValue]);

  React.useEffect(() => {
    if (notification) {
      const {
        title,
        description,
        link,
        schedule_date: scheduleDate,
        repeat,
        send_now,
      } = notification;

      if (notification.groups) {
        const groupIds = notification.groups.map(group => group.id);
        setSelectedGroups(groupIds);
      }

      setSendNow(send_now);

      setFormInitialData({
        title,
        description,
        link,
        repeat,
        send_now,
        schedule_date: scheduleDate === null ? null : getFullDate(scheduleDate),
        time: scheduleDate === null ? null : getFullHours(scheduleDate),
      });
    } else {
      /** If the form is in create mode, start in index 1 in SwipeTabs  */
      setTabIndex(1);
    }
  }, [notification]);

  const dispatchSave = () => {
    // Get all Form data input values
    const formattedNotification = {
      ...formData,
      schedule_date: sendNow
        ? null
        : createDate({
            date: formData.schedule_date,
            time: formData.time,
          }),
      send_now: sendNow,
      group_ids: selectedGroups,
      repeat: repeatValue,
      start_date: repeatValue === null ? null : startDate,
      end_date: repeatValue === null ? null : endDate,
    };

    if (!editMode) {
      dispatch(createNotification(formattedNotification));
    } else {
      dispatch(updateNotification(formattedNotification, notification.id));
    }

    onClose();
  };

  const timeInputSchema = !sendNow && {
    schedule_date: Yup.string().required(
      translate("handlers.field.isRequired")
    ),

    time: Yup.string().required(translate("handlers.field.isRequired")),
  };

  return (
    <>
      <Form
        {...rest}
        onSubmit={data => {
          setFormData(data);
          setShowModal(true);
        }}
        id="notification-form"
        onError={() => setInfoTabError(true)}
        initialData={formInitialData}
        yupValidation={{
          title: Yup.string().required(
            translate("entities.notifications.yupValidationTitle")
          ),
          description: Yup.string().required(
            translate("entities.notifications.p")
          ),
          ...timeInputSchema,
        }}
      >
        <SwipeTabs
          size="small"
          alignment="left"
          tabs={tabs}
          controlled
          index={tabIndex}
          onChange={index => setTabIndex(index)}
        >
          {notification && <PerformanceTab notification={notification} />}

          <InformationTab
            notification={notification}
            repeat={repeatValue}
            sendNow={sendNow}
            setSendNow={setSendNow}
            setRepeat={setRepeatValue}
          />

          <SegmentationTab
            selectedGroups={selectedGroups}
            setSelectedGroups={setSelectedGroups}
          />
        </SwipeTabs>
      </Form>

      <PersonalizeRepeatModal
        show={showModalPersonalize}
        notification={notification}
        setStartDate={setStartDate}
        setEndDate={setEndDate}
        setRepeat={setRepeatValue}
        onClose={() => setShowModalPersonalize(false)}
      />

      <CRUDModal
        show={showModal}
        entity={translate("entities.notifications.singular")}
        entityName={formData.title}
        isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
        type={editMode ? "update" : "create"}
        onConfirm={dispatchSave}
        onClose={() => setShowModal(false)}
        onCancel={() => onClose()}
      />
    </>
  );
}

NotificationForm.propTypes = {
  notification: PropTypes.object.isRequired,
  editMode: PropTypes.bool,
  onClose: PropTypes.func,
};

NotificationForm.defaultProps = {
  editMode: false,
  onClose: () => {},
};

export default NotificationForm;
