import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import api from "~/shared/services/api";
import { fetchRoomScheduleSuccessful, schedulesAPIError } from "./actions";
import { validResponseStatus } from "~/shared/utils/apis";

export function* fetchRoomSchedule({ payload }) {
  try {
    const { roomId } = payload;

    const response = yield call(api.get, `/schedule?broadcastRoomId=${roomId}`);

    if (validResponseStatus(response.status)) {
      const roomSchedule = response.data;

      yield put(fetchRoomScheduleSuccessful(roomSchedule));
    }
  } catch (err) {
    console.log(err);
    yield put(schedulesAPIError());
  }
}

export default all([
  takeLatest("@schedules/FETCH_ROOM_SCHEDULE", fetchRoomSchedule),
]);
