import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Card,
  IconedButton,
  Typography,
  iconedButtonPropTypes,
  typographyPropTypes,
  Box,
  Scroll,
} from "../../atoms";

const checkListCardPropTypes = {
  /** If this props is not "null", render an title above the <b>Card</b>, default is "null" */
  title: typographyPropTypes.children,

  /** If this props is not "null" render an <b>IconedButton</b> at the beginning of the card,
   * control all props of the <b>IconedButton</b>, default is "null" */
  iconedButton: PropTypes.shape(iconedButtonPropTypes),

  /** Control the <b>Card</b> styles */
  cardStyle: PropTypes.object,

  /** Control the <b>Scroll</b> styles */
  scrollStyle: PropTypes.object,

  /** Callback that is triggered when user scroll to the end of the <b>CheckList</b> */
  onBottomReached: PropTypes.func,

  children: PropTypes.element.isRequired,
};

const checkListCardDefaultProps = {
  title: null,
  iconedButton: null,
  cardStyle: null,
  scrollStyle: null,
  onBottomReached: () => {},
};

/** Just an <b>Card</b> with an <b>Scroll</b>, and <b>Title</b> and <b>Iconed</b>,
 * is commonly used to compose the <b>CheckLists</b>
 */
const ScrollableCard = ({
  title,
  iconedButton,
  cardStyle,
  scrollStyle,
  onBottomReached,
  children,
  ...rest
}) => (
  <Box {...rest}>
    {title && (
      <Typography
        className="uppercase small normal bold"
        style={{ marginBottom: 8 }}
      >
        {title}
      </Typography>
    )}

    <Card className="checkList-card" style={{ padding: 0, ...cardStyle }}>
      <Scroll
        className="checkList-scroll"
        style={scrollStyle}
        onYReachEnd={onBottomReached}
      >
        {iconedButton && (
          <IconedButton {...iconedButton} style={{ margin: 10 }} />
        )}

        {children}
      </Scroll>
    </Card>
  </Box>
);

ScrollableCard.propTypes = checkListCardPropTypes;

ScrollableCard.defaultProps = checkListCardDefaultProps;

export { ScrollableCard, checkListCardPropTypes, checkListCardDefaultProps };
