import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { CropImageModal } from "../CropImageModal";
import InputLoader from "../../molecules/cellLoaders/InputLoader";
import { Button, Typography, Input, Trans } from "../../atoms";
import {
  Container,
  Card,
  Title,
  LoaderContainer,
  Loader,
  ImagePreview,
  ImageAspect,
  FileInputArea,
} from "./styles";

const fileInputPropTypes = {
  /** Name is required to reference the FileInput inside the form  */
  name: PropTypes.string.isRequired,

  /** Add an title above input */
  title: PropTypes.string,

  /** The value that will be displayed into preview */
  src: PropTypes.string.isRequired,

  /** If TRUE display an InputLoader instead of the FileInput */
  isLoading: PropTypes.bool,

  /** Control the image width in px, default is 300ox, his metric
   *  combine with heigth create the image aspect to the CropImage */
  width: PropTypes.number,

  /** Control the image height in px, default is 168px, This metric
   *  combine with width create the image aspect to the CropImage */
  heigth: PropTypes.number,

  /** Callback that is triggered when user upload an file, return the file uploaded in
   * <b>FormData</b>.
   *
   * Example: (file) => {}
   */
  onUpload: PropTypes.func,

  /** If <b>true</b>, render an <b>Spinner</b> instead of show the file into preview.
   * default value is <b>falkse</b>
   */
  isUploading: PropTypes.bool,
};

const fileInputDefaultProps = {
  title: "",
  isLoading: false,
  width: 300,
  heigth: 168,
  onUpload: () => {},
  isUploading: false,
};

/** FileInput needs to be inside an <b>Form</b> component because the component
 * anotates the value of the <b>src</b> props.
 */
const FileInput = ({
  src,
  name,
  title,
  width,
  heigth,
  onUpload,
  isLoading,
  isUploading,
  ...rest
}) => {
  //* STATES
  const [cropPreview, setCropPreview] = React.useState(null);
  const [showCrop, setShowCrop] = React.useState(false);
  const heightAspect = heigth / width;

  const handleInputChange = e => {
    const binaryData = [];
    binaryData.push(e.target.files[0]);
    const file = window.URL.createObjectURL(
      new Blob(binaryData, { type: "application/zip" })
    );

    setCropPreview(file);
    setShowCrop(true);
  };

  const handleUploadCroppedImage = file => {
    const data = new FormData();
    data.append("file", file);
    onUpload(data);
  };

  return isLoading ? (
    <InputLoader />
  ) : (
    <>
      <Container {...rest}>
        <Title>{title}</Title>

        <Card>
          {isUploading ? (
            <LoaderContainer className="file-input-image-preview">
              <Loader animation="border" />
            </LoaderContainer>
          ) : (
            <ImagePreview
              heightAspect={heightAspect}
              className="file-input-image-preview"
              src={src}
            >
              {!src && (
                <ImageAspect>
                  {width} x {heigth}
                </ImageAspect>
              )}
            </ImagePreview>
          )}

          <FileInputArea>
            <Input className="file-input" name={name} value={src} />

            <input
              className="file-input"
              type="file"
              id={name}
              accept="image/*"
              onChange={e => handleInputChange(e)}
            />

            <Typography className="small opaque bold" maxLines={2}>
              {src || (
                <Trans translationKey="components.fileInput.placeholder">
                  Selecionar arquivo (JPG | PNG)
                </Trans>
              )}
            </Typography>

            <Button
              disableElevation
              size="small"
              onClick={() => {
                document.getElementById(name).click();
              }}
            >
              <Trans translationKey="components.fileInput.searchButton">
                Pesquisar
              </Trans>
            </Button>
          </FileInputArea>
        </Card>
      </Container>

      <CropImageModal
        show={showCrop}
        onClose={() => setShowCrop(false)}
        src={cropPreview}
        widthAspect={1}
        heightAspect={heightAspect}
        fileName="file"
        onSave={handleUploadCroppedImage}
      />
    </>
  );
};

FileInput.propTypes = fileInputPropTypes;

FileInput.defaultProps = fileInputDefaultProps;

export { FileInput, fileInputPropTypes, fileInputDefaultProps };
