import React from "react";
import PropTypes from "prop-types";
import { MdAddCircle } from "react-icons/md";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const iconedButtonPropTypes = {
  /** Define the icon for the button, default is an <b>MdAddCircle</b> */
  icon: PropTypes.element,

  /** An string to be displayed next to icon */
  children: PropTypes.node,

  /** An string to be displayed next to icon */
  placeholder: PropTypes.string,
};

const iconedButtondefaultProps = {
  icon: null,
  children: null,
  placeholder: null,
};

/** An icon with an label, used more for CMS layout */
function IconedButton({ placeholder, children, icon, ...rest }) {
  return (
    <Container type="button" {...rest}>
      <div className="icon">{icon || <MdAddCircle />}</div>
      {children || placeholder}
    </Container>
  );
}

IconedButton.propTypes = iconedButtonPropTypes;

IconedButton.defaultProps = iconedButtondefaultProps;

export { IconedButton, iconedButtonPropTypes, iconedButtondefaultProps };
