import React from "react";
import PropTypes from "prop-types";

// APIS
import { createActiveLog } from "~/modules/signed/modules/client/apis/activeLog";

// CUSTOM IMPORTS
import ExtraButtonViewList from "~/shared/components/organisms/lists/ExtraButtonViewList";
import { ACTIVE_LOG_TYPES } from "~/shared/utils/types";

function BroadcastRoomExtraButtonList({ extraButtons }) {
  const handleExtraButtonClick = contactButton => {
    createActiveLog({
      action: ACTIVE_LOG_TYPES.BROADCAST_ROOM_EXTRA_BUTTON,
      refId: contactButton.id,
    });
  };

  return (
    <ExtraButtonViewList
      extraButtons={extraButtons}
      onClick={handleExtraButtonClick}
    />
  );
}

BroadcastRoomExtraButtonList.propTypes = {
  extraButtons: PropTypes.any,
};

BroadcastRoomExtraButtonList.defaultProps = {
  extraButtons: null,
};

export default BroadcastRoomExtraButtonList;
