import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import { listMarketRooms } from "~/modules/signed/modules/cms/modules/admin/store/marketRooms/actions";

//* CUSTOM IMPORTS
import Loader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import {
  CheckListItem,
  ScrollableCard,
  CellList,
  Empty,
} from "~/shared/components/molecules";
import { Typography, Icon, Box } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const PartnerCheckList = ({
  title,
  selectedPartners,
  setSelectedPartners,
  ...rest
}) => {
  const { dTranslateTags, dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  //* REDUCER
  const dispatch = useDispatch();
  const marketRoomsReducer = useSelector(state => state.adminMarketRooms.list);
  const { data: marketRooms, isLoaded } =
    marketRoomsReducer && marketRoomsReducer;

  //* FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listMarketRooms());

    return () => {
      isMounted = false;
    };
  }, [dispatch]);

  function handleSelectSpeaker(speakerId) {
    // Find the index of id
    const alreadySelected = selectedPartners.findIndex(
      item => item === speakerId
    );

    // If do not have de id inside array will return - 1
    if (alreadySelected >= 0) {
      // Receive all the array less the id
      const filteredTags = selectedPartners.filter(item => item !== speakerId);

      setSelectedPartners(filteredTags);
    } else {
      setSelectedPartners();
      setSelectedPartners([...selectedPartners, speakerId]);
    }
  }

  return (
    <ScrollableCard
      scrollStyle={{ maxHeight: 400 }}
      cardStyle={{ minWidth: 350 }}
      title={title || translate("entities.sponsors.title")}
      {...rest}
    >
      <CellList
        orientation="column"
        isLoaded={isLoaded}
        renderCellLoader={() => <Loader style={{ margin: 10, width: "85%" }} />}
        data={marketRooms}
        renderCell={marketRoom => {
          const { partners } = marketRoom;

          return (
            <Box>
              <Typography
                className="tiny uppercase normal bold"
                style={{ padding: 10 }}
              >
                {dTranslate(marketRoom.name)}
              </Typography>

              <CellList
                key={marketRoom.id}
                orientation="column"
                isLoaded
                data={partners}
                renderCell={partner => (
                  <CheckListItem
                    variant="avataredTagged"
                    onClick={() => handleSelectSpeaker(partner.id)}
                    key={partner.id}
                    title={dTranslate(partner.title)}
                    avatar={partner.logo}
                    tags={dTranslateTags(partner.tags)}
                    checkbox={{
                      checked: !!selectedPartners.includes(partner.id),
                      onClick: () => handleSelectSpeaker(partner.id),
                    }}
                  />
                )}
                emptyComponent={
                  <Empty
                    message={translate("entities.sponsors.empty.message")}
                    description={translate(
                      "entities.sponsors.empty.description"
                    )}
                    size="small"
                    style={{ padding: "0 10px 10px", alignItems: "flex-start" }}
                  />
                }
              />
            </Box>
          );
        }}
        emptyComponent={
          <Empty
            icon={<Icon iconName="person" />}
            message={translate("entities.sponsors.empty.message")}
            description={translate("entities.sponsors.empty.description")}
            style={{ padding: 20 }}
          />
        }
      />
    </ScrollableCard>
  );
};

PartnerCheckList.propTypes = {
  title: PropTypes.string,
  className: PropTypes.string,
  selectedPartners: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedPartners: PropTypes.func.isRequired,
};

PartnerCheckList.defaultProps = {
  title: null,
  className: "partner-checklist",
};

export { PartnerCheckList };
