import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import { Dropdown } from "~/shared/components/organisms";
import { IconedButton } from "~/shared/components/atoms";
import { Modal, Form, FooterButtons } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useImportUsers } from "~/modules/signed/modules/cms/modules/admin/pages/Users/pages/ImportUsers/hooks";
import { removeSnakeCase, capitalize } from "~/shared/utils/strings";
import { openRegistrationFieldModal } from "~/shared/routes/navigation";
import { customFields } from "~/shared/utils/inputs";

const columnPropTypes = PropTypes.shape({
  name: PropTypes.string,
  //* *This is the cadaster item that corresponds this column */
  correspondingField: PropTypes.object,
});

const columnModalPropTypes = {
  column: columnPropTypes,
  columns: PropTypes.arrayOf(columnPropTypes),
  onClose: PropTypes.func.isRequired,
};

const columnModalDefaultProps = {
  column: null,
  columns: [],
};

const ColumnModal = ({ column, onClose, ...rest }) => {
  const { translate } = useTranslation();

  const {
    hasRelatedColumn,
    relateFieldToColumn,
    cadasterItems,
  } = useImportUsers();

  //* STATES
  const [selectedField, setSelectedField] = React.useState(
    column ? column.correspondingField : null
  );

  //* FUNCTIONS
  if (!column) return null;

  const { correspondingField } = column;

  return (
    <Modal
      onClose={onClose}
      title={`${translate("common.editColumn")} ${capitalize(
        removeSnakeCase(column.name)
      )}`}
      {...rest}
      style={{ width: "100%", maxWidth: 500 }}
    >
      <Form
        onSubmit={() => {
          relateFieldToColumn(selectedField, column);
          onClose();
        }}
        yupValidation={{
          correspondingField: Yup.string()
            .test(
              "Field has already been matched",
              `${translate(
                "pages.userImportMatchInfo.columnModal.fieldAlreadyRelatedMessage.part1"
              )} ${selectedField &&
                capitalize(selectedField.title)} ${translate(
                "pages.userImportMatchInfo.columnModal.fieldAlreadyRelatedMessage.part2"
              )}
              `,
              () => {
                //* *If field already has an related column dispatch an error */
                if (hasRelatedColumn(selectedField, column)) return false;

                return true;
              }
            )
            .required(translate("handlers.matchColumn.isRequired")),
        }}
      >
        <Dropdown
          initialValue={correspondingField && correspondingField.title}
          options={cadasterItems.map(cadasterItem => ({
            id: cadasterItem.id,
            name: cadasterItem.title,
            value: cadasterItem.ref_code,
            ...cadasterItem,
          }))}
          optionHeaderComponent={
            <IconedButton
              onClick={() => openRegistrationFieldModal(customFields[0], false)}
              placeholder={translate("entities.registrationFields.add")}
              style={{ margin: "8px 16px" }}
            />
          }
          onChange={(_, option) => setSelectedField(option)}
          style={{ width: "100%" }}
          title={translate("components.select.matchColumn.title")}
          placeholder={translate("components.select.matchColumn.placeholder")}
          name="correspondingField"
        />

        <FooterButtons type="submit" marginTop="20px" onCancel={onClose} />
      </Form>
    </Modal>
  );
};

ColumnModal.propTypes = columnModalPropTypes;

ColumnModal.defaultProps = columnModalDefaultProps;

export default ColumnModal;
