import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getMarketRoomsReportCountAccess } from "~/modules/signed/modules/cms/modules/admin/apis/marketRooms";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { DashboardAccessReportCard } from "~/shared/components/molecules";
import {
  openAdminMarketRooms,
  openAdminMarketRoomsAccess,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function MarketRoomsAccessCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: marketRoomsAccessReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getMarketRoomsReportCountAccess({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardAccessReportCard
      isLoaded={isLoaded}
      title={translate("entities.marketRooms.boothAccesses")}
      dotColor="#DC2BFD"
      accessReportItems={
        isLoaded &&
        marketRoomsAccessReport.data.map(marketRoom => ({
          ...marketRoom,
          title: dTranslate(marketRoom.title),
        }))
      }
      emptyMessage={translate("entities.marketRooms.empty.message")}
      onEmptyButtonClick={openAdminMarketRooms}
      emptyButtonPlaceholder={translate("entities.marketRooms.createBooth")}
      onLinkClick={() =>
        openAdminMarketRoomsAccess({
          startDate: dateInterval.startDate,
          endDate: dateInterval.endDate,
          timezone,
        })
      }
    />
  );
}

MarketRoomsAccessCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
  timezone: PropTypes.number.isRequired,
};

export default MarketRoomsAccessCard;
