/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import {
  fetchTransmissionRoomSuccessful,
  setIsBroadcastRoomPrivate,
  sendRoomQuestionSuccessful,
  fetchSponsoredRoomsSuccessful,
  fetchTransmissionRoomRateSuccessful,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

export function* fetchSponsoredRooms({ payload }) {
  try {
    const { partnerId } = payload;

    const response = yield call(
      api.get,
      `/partnerbroadcast?partnerId=${partnerId}`
    );

    if (validResponseStatus(response.status)) {
      const sponsoredRooms = response.data;

      yield put(fetchSponsoredRoomsSuccessful(sponsoredRooms));
    }
  } catch (err) {
    console.log(err);
  }
}

export function* fetchTransmissionRoom({ payload }) {
  try {
    const { roomId } = payload;

    const response = yield call(api.get, `/broadcastroom/${roomId}`);

    if (validResponseStatus(response.status)) {
      const transmissionRoom = response.data;

      yield put(fetchTransmissionRoomSuccessful(transmissionRoom));
    }
  } catch (err) {
    if (err.response.status === 403) yield put(setIsBroadcastRoomPrivate());
    console.log(err);
  }
}

export function* sendRoomQuestion({ payload }) {
  try {
    const { roomId, question } = payload;

    const response = yield api.post("question", {
      question,
      broadcast_room_id: roomId,
    });

    if (validResponseStatus(response.status)) {
      yield put(sendRoomQuestionSuccessful());
    }
  } catch (err) {
    console.log(err);
  }
}

export function* evaluateTransmissionRoom({ payload }) {
  try {
    const { roomId: room_id, rate, description } = payload;

    yield api.post("/rateroom", {
      room_id,
      description,
      rate,
    });
  } catch (err) {
    console.log(err);
  }
}

export function* fetchTransmissionRoomRate({ payload }) {
  try {
    const { roomId } = payload;

    const response = yield api.get(`/rateroom/${roomId}`);

    if (validResponseStatus(response.status)) {
      const { rate, description } = response.data;

      yield put(fetchTransmissionRoomRateSuccessful({ rate, description }));
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest("@rooms/FETCH_TRANSMISSION_ROOM", fetchTransmissionRoom),
  takeLatest("@rooms/FETCH_SPONSORED_ROOMS", fetchSponsoredRooms),
  takeLatest("@rooms/SEND_ROOM_QUESTION", sendRoomQuestion),
  takeLatest("@rooms/EVALUATE_TRANSMISSION_ROOM", evaluateTransmissionRoom),
  takeLatest("@rooms/FETCH_TRANSMISSION_ROOM_RATE", fetchTransmissionRoomRate),
]);
