import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  createRegistrationField,
  updateRegistrationField,
} from "~/modules/signed/modules/cms/modules/admin/store/registrationFields/actions";

//* CUSTOM IMPORTS
import { popState } from "~/shared/services/history";
import { isDataValid } from "~/shared/utils/validations";
import Icons from "~/modules/signed/modules/cms/components/lists/IconList";
import {
  inputOptionType,
  convertInputType,
  INPUT_TYPES,
} from "~/shared/utils/inputs";
import { useTranslation } from "~/shared/hooks/translation";
import { Typography, Trans } from "~/shared/components/atoms";
import {
  FormModal,
  Input,
  Checkbox,
  CheckboxFieldset,
  CheckboxArea,
  Dropdown,
  DropdownAttributes,
} from "./styles";
import { AdminTranslateInput as TranslateInput } from "../../AdminTranslateInput";

function RegistrationFieldModal({ registrationField, editMode }) {
  const dispatch = useDispatch();
  const { isCreating, isUpdating } = useSelector(
    state => state.adminRegistrationFields.list
  );

  //* STATES
  const hasField = isDataValid(registrationField);
  const [initialData, setInitialData] = React.useState({});
  const [isRequired, setIsRequired] = React.useState(false);
  const [isPresetField, setIsPresetField] = React.useState(false);
  const [selectedIcon, setSelectedIcon] = React.useState(null);
  const [type, setType] = React.useState(
    hasField ? registrationField.type : null
  );
  const typeName = convertInputType({ typeValue: type });
  const [attributes, setAttributes] = React.useState([]);

  const hasIcons = type !== INPUT_TYPES.TAGS.value;
  const hasPlaceholder = type !== INPUT_TYPES.TAGS.value;
  const hasAttributes =
    type === INPUT_TYPES.SELECT.value || type === INPUT_TYPES.TAGS.value;
  const hasMin = type === INPUT_TYPES.TAGS.value;
  const hasMax = type === INPUT_TYPES.TAGS.value;
  const hasIsRequired = type !== INPUT_TYPES.TAGS.value && !isPresetField;

  //* FUNCTIONS
  const { translate } = useTranslation();

  const setYupValidation = () => {
    const yupShape = {};

    //* * Add validation to attributes list*/
    if (hasAttributes)
      attributes.forEach(attribute => {
        const formmatedAttrName = String(attribute.title)
          .toLocaleLowerCase()
          .trim();

        yupShape[formmatedAttrName] = Yup.string().required(
          translate("handlers.options.isRequired")
        );
      });

    if (hasPlaceholder)
      yupShape.placeholder = Yup.string().required(
        translate("handlers.hintPhrase.isRequired")
      );

    return {
      ...yupShape,
      title: Yup.string().required(translate("handlers.field.isRequired")),
    };
  };

  const handleSubmit = data => {
    const registrationFieldData = {
      ...data,
      min: parseInt(data.min, 10),
      max: parseInt(data.max, 10),
      type,
      icon_type: selectedIcon,
      required: isRequired,
      order: editMode ? registrationField.order : 0,
      dropdown_attributes: attributes.map(a => {
        if (a.isNewField) return { ...a, id: null };

        return a;
      }),
    };

    if (editMode) {
      dispatch(
        updateRegistrationField({
          registrationField: registrationFieldData,
          registrationFieldId: registrationField.id,
        })
      );
    } else {
      dispatch(
        createRegistrationField({
          registrationField: registrationFieldData,
        })
      );
    }
  };

  React.useEffect(() => {
    if (hasField) {
      const { required, icon_type, deletable } = registrationField;

      if (!deletable) setIsPresetField(true);

      setIsRequired(required);
      setSelectedIcon(icon_type);
      setInitialData(registrationField);
    }
  }, [editMode, hasField, registrationField]);

  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH") popState();
  }, [isCreating, isUpdating]);

  return (
    <FormModal
      show
      onClose={popState}
      withScroll
      isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
      isPresetField={isPresetField}
      initialData={initialData}
      yupValidation={setYupValidation()}
      onSubmit={handleSubmit}
      title={
        editMode
          ? translate("entities.registrationFields.edit")
          : translate("entities.registrationFields.create")
      }
    >
      {!isPresetField && (
        <Dropdown
          title={translate("entities.registrationFields.type")}
          name="type"
          options={inputOptionType}
          onChange={value => setType(value)}
          initialValue={typeName}
          style={{ width: "calc(50% - 16px)", marginRight: 16 }}
        />
      )}

      <TranslateInput
        name="title"
        title={translate("components.input.field.title")}
        placeholder={translate("components.input.field.placeholder")}
        style={{ marginBottom: 16, width: "50%" }}
      />

      {hasPlaceholder && (
        <TranslateInput
          name="placeholder"
          title={translate("components.input.hintPhrase.title")}
          placeholder={translate("components.input.hintPhrase.placeholder")}
          style={{ marginBottom: 16, width: "100%" }}
        />
      )}

      {hasMin && (
        <Input
          type="number"
          name="min"
          max={attributes.length}
          min={0}
          title={translate("components.input.min.title")}
          placeholder={translate("components.input.min.placeholder")}
          onChange={e => {
            const { value } = e.target;

            const maxInput = document.getElementById("max");

            if (maxInput) maxInput.setAttribute("min", value);
          }}
        />
      )}

      {hasMax && (
        <Input
          type="number"
          name="max"
          id="max"
          max={attributes.length}
          min={0}
          title={translate("components.input.max.title")}
          placeholder={translate("components.input.max.placeholder")}
        />
      )}

      {hasAttributes && (
        <DropdownAttributes
          editMode={editMode}
          attributes={attributes}
          setAttributes={setAttributes}
          refCode={editMode && registrationField.ref_code}
        />
      )}

      {hasIsRequired && (
        <CheckboxArea>
          <CheckboxFieldset>
            <Checkbox
              checked={isRequired}
              onClick={() => setIsRequired(!isRequired)}
            />

            <Typography>
              <Trans translationKey="entities.registrationFields.mandatory">
                Campo Obrigatório
              </Trans>
            </Typography>
          </CheckboxFieldset>
        </CheckboxArea>
      )}

      {hasIcons && (
        <Icons setSelectedIcon={setSelectedIcon} selectedIcon={selectedIcon} />
      )}
    </FormModal>
  );
}

RegistrationFieldModal.propTypes = {
  registrationField: PropTypes.object,
  editMode: PropTypes.any,
};

RegistrationFieldModal.defaultProps = {
  registrationField: null,
  editMode: null,
};

export default RegistrationFieldModal;
