import React, { createContext, useCallback } from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useSocket } from "../../../../../../../hooks/webSocket";

const ImportSocketContext = createContext(null);

const IMPORT_STATUS = {
  STARTED: "started",
  FINISHED: "finished",
};

const ImportSocketProvider = ({ children }) => {
  const { subscribe, closeChannel } = useSocket();

  //* STATES
  const [completedImports, setCompletedImports] = React.useState([]);
  const [importsProgress, setImportsProgress] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    const socket = subscribe(`import:all`);

    if (socket) {
      socket.on("import_finished", completedImportData =>
        setCompletedImports([...completedImports, completedImportData])
      );

      socket.on("on_progress", data => {
        if (!data) return;

        const { import_id: importId, import_percent: importPercent } = data;

        // eslint-disable-next-line no-unused-vars
        const [formmatedPercent, _] = String(importPercent).split(".");

        if (importsProgress.length > 0) {
          const auxImportsProgress = importsProgress;

          const index = importsProgress.findIndex(i => i.importId === importId);

          if (index)
            auxImportsProgress[index] = {
              importId,
              importPercent:
                auxImportsProgress[index].importPercent + formmatedPercent,
            };

          setImportsProgress(auxImportsProgress);
        } else {
          setImportsProgress([{ importId, importPercent: formmatedPercent }]);
        }
      });
    }

    return () => {
      closeChannel(socket);
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [subscribe]);

  const onImportProgress = useCallback(
    importId => {
      const importProgress = importsProgress.find(
        i => Number(i.importId) === Number(importId)
      );

      if (importProgress) return importProgress.importPercent;

      return 0;
    },
    [importsProgress]
  );

  const onImportFinished = useCallback(
    importId => {
      const completedImportStatus = completedImports.find(
        i => Number(i.id) === Number(importId)
      );

      return completedImportStatus || null;
    },
    [completedImports]
  );

  return (
    <ImportSocketContext.Provider
      value={{ onImportFinished, onImportProgress }}
    >
      {children}
    </ImportSocketContext.Provider>
  );
};

ImportSocketProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useImportSocket() {
  const context = React.useContext(ImportSocketContext);

  if (!context)
    throw new Error(
      "useImportSocket must be used within ImportSocketProvider and SocketProvider"
    );

  return context;
}

export { ImportSocketProvider, useImportSocket, IMPORT_STATUS };
