import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Icon, Box, iconPropTypes } from "../../atoms";
import { Line, Button, ButtonContainer } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const extendLinePropTypes = {
  /** Control the label for the <b>Button</b>, if this props is
   * empty by default the label is "show more".
   */
  label: PropTypes.string,

  icon: PropTypes.shape(iconPropTypes),

  onClick: PropTypes.func,
};

const extendLineDefaultProps = {
  icon: null,
  label: null,
  onClick: () => {},
};

const LineButton = ({ onClick, label, icon, ...rest }) => {
  const { translate } = useTranslation();

  return (
    <Line {...rest}>
      <ButtonContainer className="line-button-cointainer">
        <Button variant="text" onClick={onClick}>
          <Box display="flex">
            {label || translate("common.showMore")}
            <Icon
              iconName="arrowDown"
              variant="accent"
              style={{ marginLeft: 4 }}
              {...icon}
            />
          </Box>
        </Button>
      </ButtonContainer>
    </Line>
  );
};

LineButton.propTypes = extendLinePropTypes;

LineButton.defaultProps = extendLineDefaultProps;

export { LineButton };
