import React from "react";
import PropTypes from "prop-types";

//* APIs
import { createUserMeeting } from "../../../../apis/meetings";

//* CUSTOM IMPORTS
import { SwipeTabs } from "~/shared/components/molecules";
import { TagSchedule } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { MEETING_TYPES } from "~/shared/utils/types";
import {
  getFullDate,
  getFullHours,
  getTimeCourse,
  TIME_COURSE,
} from "~/shared/utils/dates";
import { DialogModal } from "./styles";
import { useAlert } from "~/shared/hooks/alert";

const TABS = {
  SELECT_DAY: 0,
  SELECT_TIME: 1,
};

const meetingModalPropTypes = {
  partner: PropTypes.object.isRequired,
  /** Function that closes the MeetingModal */
  onClose: PropTypes.func.isRequired,
  availableDates: PropTypes.array.isRequired,
  isLoaded: PropTypes.bool.isRequired,
  /** Callback when user creates an meeting, return the meeting data */
  onCreate: PropTypes.func.isRequired,
};

const MeetingModal = ({
  partner,
  onClose,
  onCreate,
  availableDates,
  isLoaded,
  ...rest
}) => {
  const { translate } = useTranslation();
  const { showAlert } = useAlert();

  //* STATES
  const [tabIndex, setTabIndex] = React.useState(TABS.SELECT_DAY);
  const [isSaving, setIsSaving] = React.useState(false);
  const [selectedDay, setSelectedDay] = React.useState(null);
  const [selectedHour, setSelectedHour] = React.useState(null);

  const isDayTab = tabIndex === TABS.SELECT_DAY;

  //* FUNCTIONS
  const handleOnCancel = () => {
    onClose();
    setSelectedDay(null);
    setSelectedHour(null);
    setTabIndex(TABS.SELECT_DAY);
  };

  const handleSelectDay = tag => {
    if (selectedDay)
      if (tag.id === selectedDay.id) {
        setSelectedDay(null);
        return;
      }

    setSelectedDay(tag);
  };

  const handleSelectTime = tag => {
    if (selectedHour)
      if (tag.id === selectedHour.id) {
        setSelectedHour(null);
        return;
      }

    setSelectedHour(tag);
  };

  const handleOnAdvance = () => {
    setTabIndex(TABS.SELECT_TIME);
  };

  const handleOnReturn = () => setTabIndex(TABS.SELECT_DAY);

  const handleOnSave = async () => {
    const { hour } = selectedHour;
    const { start_date: startDate, end_date: endDate } = hour;

    setIsSaving(true);

    await createUserMeeting({
      startDate,
      endDate,
      partnerId: partner.id,
      playerType: MEETING_TYPES.JAAS,
    })
      .then(response => {
        setIsSaving(false);
        onCreate(response.data);
      })
      .catch(() => setIsSaving(false));
  };

  const showConfirmAlert = () => {
    const { name: dayName } = selectedDay;
    const { hour } = selectedHour;

    showAlert({
      title: translate("entities.meetings.confirm"),
      description: (
        <>
          {translate("entities.meetings.confirmMessage.part1")}
          <strong>{partner.title}</strong>{" "}
          {translate("entities.meetings.confirmMessage.part2")}{" "}
          <strong>{dayName}</strong> {translate("common.at")}{" "}
          <strong>{getFullHours(hour.start_date)}</strong>,{" "}
          {translate("entities.meetings.confirmMessage.part3")}
        </>
      ),
      onConfirm: handleOnSave,
    });
  };

  const availableDays = React.useMemo(
    () => [
      {
        id: 1,
        data: availableDates.map((date, index) => ({
          id: index,
          date: date.day,
          name: getFullDate(date.day, true),
          available_hours: date.available_hours,
          isSelected: selectedDay ? selectedDay.id === index : false,
        })),
      },
    ],
    [availableDates, selectedDay]
  );

  const availableHours = React.useMemo(() => {
    if (!selectedDay) return [];

    const morning = [];
    const evening = [];
    const night = [];
    const dawn = [];

    selectedDay.available_hours.forEach((hour, index) => {
      const {
        start_date: startDate,
        end_date: endDate,
        is_used: isUsed,
      } = hour;
      const { EVENING, MORNING, NIGHT } = TIME_COURSE;

      const hourRange = {
        id: index,
        name: `${getFullHours(startDate)} ${translate(
          "common.at"
        )} ${getFullHours(endDate)}`,
        hour,
        disabled: isUsed,
        isSelected: selectedHour ? selectedHour.id === index : false,
      };

      /** Check the startDAte timeCourse then add into it respective array. */
      switch (getTimeCourse(startDate)) {
        case MORNING:
          morning.push(hourRange);
          break;

        case EVENING:
          evening.push(hourRange);
          break;

        case NIGHT:
          night.push(hourRange);
          break;

        default:
          dawn.push(hourRange);
          break;
      }
    });

    const avaiableTimeCourser = [];

    if (dawn.length)
      avaiableTimeCourser.push({
        id: 1,
        title: translate("common.dawn"),
        data: dawn,
      });

    if (morning.length)
      avaiableTimeCourser.push({
        id: 1,
        title: translate("common.morning"),
        data: morning,
      });

    if (evening.length)
      avaiableTimeCourser.push({
        id: 1,
        title: translate("common.evening"),
        data: evening,
      });

    if (night.length)
      avaiableTimeCourser.push({
        id: 1,
        title: translate("common.night"),
        data: night,
      });

    return avaiableTimeCourser;
  }, [selectedDay, selectedHour, translate]);

  return (
    <DialogModal
      {...rest}
      onClose={onClose}
      isLoading={isSaving}
      onCancel={handleOnCancel}
      onReturn={!isDayTab && handleOnReturn}
      title={translate("common.scheduleMeeting")}
      isDisable={isDayTab ? !selectedDay : !selectedHour}
      onConfirm={isDayTab ? handleOnAdvance : showConfirmAlert}
      confirmButton={translate(isDayTab ? "common.advance" : "common.save")}
    >
      <SwipeTabs
        disableTabs
        controlled
        index={tabIndex}
        style={{ marginTop: 8 }}
        childrenStyle={{ padding: "8px 16px" }}
      >
        <TagSchedule
          data={availableDays}
          isLoading={!isLoaded}
          onClick={handleSelectDay}
          title={translate("common.chooseADay")}
        />

        {!isDayTab && (
          <TagSchedule
            withScroll
            scrollStyle={{ maxHeight: 350 }}
            data={availableHours}
            onClick={handleSelectTime}
            title={translate("common.chooseATime")}
          />
        )}
      </SwipeTabs>
    </DialogModal>
  );
};

MeetingModal.propTypes = meetingModalPropTypes;

export { MeetingModal };
