import api from "~/shared/services/api";

export const updateConfig = data => api.put("/admin/gamification_config", data);

export const getConfig = () => api.get("/admin/gamification_config");

export const getRanking = (page, search) =>
  api.get("/admin/ranking", { params: { page, search } });

export const resetScore = () => api.put("admin/gamification_config/reset");

export const updateUserScore = (userId, points) =>
  api.put("/admin/user_point/update", { user_id: userId, points });

export const resetUserScore = userId =>
  api.put("admin/user_point/reset", { user_id: userId });

export const getUserExtractPointsHistoric = ({ userId, page }) =>
  api.get(`/admin/user_point/history/${userId}`, { params: { page } });

export const getUserPoints = ({ userId }) =>
  api.get(`/admin/user_point/${userId}`);
