import styled, { css } from "styled-components";

export const Button = styled.button`
  display: flex;
  flex-direction: column;
  background: none;
  border-radius: 8px;
  justify-content: ${props => props.justifyContent};
  padding: 16px;
  align-items: center;
  width: 100%;
  border: 2px solid ${props => props.theme.colors.shadow};

  svg {
    height: 52px;
    width: 52px;
    color: ${props => props.theme.colors.shadow};
    margin-bottom: 10px;
  }

  h4 {
    margin: 0;
    font-size: 16px;
    font-weight: bold;
    color: ${props => props.theme.colors.opaque};
  }

  ${props =>
    props.direction === "row" &&
    css`
      flex-direction: row;

      svg {
        margin-bottom: 0;
        margin-right: 20px;
      }
    `}

  ${props =>
    props.isActive &&
    css`
    h4,
    svg {
      color: #fff;
    }
    background: ${props => props.theme.colors.accent};
    border: 2px solid ${props => props.theme.colors.accent};
    box-shadow: 2px 5px 6px ${props => props.theme.colors.shadow};
    transition: background 0.2s ease;
  }
    `}

  &:hover,
  &:active {
    h4,
    svg {
      color: #fff;
    }
    background: ${props => props.theme.colors.accent};
    border: 2px solid ${props => props.theme.colors.accent};
    box-shadow: 2px 5px 6px ${props => props.theme.colors.shadow};
    transition: background 0.2s ease;
  }
`;
