import types from "./types";

// BLOCK
export function blockUser({ blockData }) {
  return {
    type: types.BLOCK.REQUEST,
    payload: { blockData },
  };
}
export function blockUserSuccessful() {
  return {
    type: types.BLOCK.SUCCESSFUL,
  };
}
export function blockUserFailed() {
  return {
    type: types.BLOCK.FAILED,
  };
}
