import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import { AccessDetailsTemplate } from "~/shared/components/templates";
import { useTranslation } from "~/shared/hooks/translation";
import { Typography } from "~/shared/components/atoms";
import { useAdminEmail } from "../../../../hooks";

function EmailPerformance() {
  // STATES
  const { emailId } = useParams();
  const { adminEmailLog, log } = useAdminEmail();
  const { translate } = useTranslation();
  const [pagination, setPagination] = React.useState({
    page: 1,
    lastPage: log.lastPage,
  });

  React.useEffect(() => {
    adminEmailLog(emailId, pagination.page);
  }, [emailId, pagination.page, adminEmailLog]);

  // FUNCTIONS
  const renderTableRows = () => {
    return log.data.map(row => {
      if (!row) return null;

      return {
        user: (
          <Typography className="accent subTitle bold">
            {row.user.name}
          </Typography>
        ),
        email: <Typography className="subTitle">{row.user.email}</Typography>,
      };
    });
  };

  return (
    <AccessDetailsTemplate
      hasSearchBar={false}
      hasDateSelect={false}
      cardData={{
        title: translate("entities.emails.emailDetails.report").toUpperCase(),
        dotColor: "#2BFD6A",
        isLoaded: true,
        data: [
          {
            id: 1,
            counter: log.total,
            description: translate(
              "entities.emails.emailDetails.received"
            ).toUpperCase(),
          },
        ],
      }}
      tableData={{
        rows: renderTableRows(),
        titles: [translate("common.user"), translate("common.email")],
      }}
      tablePagination={{
        currentPage: pagination.page,
        lastPage: pagination.lastPage,
        handlePagination: page =>
          setPagination({
            page,
            lastPage: log.lastPage,
          }),
      }}
    />
  );
}

EmailPerformance.propTypes = {};

export default EmailPerformance;
