import storage from "redux-persist/lib/storage";
import { persistReducer } from "redux-persist";

export default reducers => {
  const persistedReducer = persistReducer(
    {
      key: "yazo-dev",
      storage,
      blacklist: [
        //* USER
        "networkRooms",
        "marketRooms",
        "broadcastRooms",
        "rooms",
        "chat",
        "schedules",
        "rates",
        "registrationFields",

        //* ADMIN
        "adminBroadcastRooms",
        "adminTags",
        "adminSchedule",
        "adminSpeakers",
        "adminMarketRooms",
        "adminPartners",
        "adminExtraLinks",
        "adminNotifications",
        "adminNetworkRooms",
        "certificates",
        "adminGroups",
        "adminUsers",
        "adminBanners",
        "moderations",
        "adminCertificates",

        //* WEB SOCKET
        "wsBroadcastRoomChat",
        "wsReactions",
      ],
    },
    reducers
  );

  return persistedReducer;
};
