import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";

import { Typography } from "~/shared/components/atoms";
import { useImportUsers } from "~/modules/signed/modules/cms/modules/admin/pages/Users/pages/ImportUsers/hooks";
import { ImportErrorsModal } from "~/shared/components/organisms";

const errorsModalPropTypes = {
  onClose: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
};

function ErrorsModal({ onSave, onClose, ...rest }) {
  const {
    sheetData,
    rowLoading,
    importErrors,
    cadasterItems,
    updateSheetCellData,
    sheetDataHasChanges,
    errorsImportToTableData,
    errorsImportMessageToString,
    validateSheetImportSingleData,
  } = useImportUsers();

  //* FUNCTIONS
  const { translate } = useTranslation();

  return (
    <ImportErrorsModal
      {...rest}
      onClose={onClose}
      onChange={(inputValue, value, rowIndex) => {
        if (!value) return;

        const { lineErrorIndex, refCode } = value;

        updateSheetCellData(inputValue, lineErrorIndex, refCode);

        const line = sheetData[lineErrorIndex];

        line[rowIndex] = inputValue;

        validateSheetImportSingleData(line, rowIndex);
      }}
      rowLoadingId={rowLoading}
      title={translate("entities.users.withErrors")}
      titles={cadasterItems.map(item => item.title)}
      data={errorsImportToTableData(importErrors)}
      errorMessages={errorsImportMessageToString(importErrors)}
      emptyValueComponent={
        <Typography className="opaque italic">
          {translate("common.unavailable")}
        </Typography>
      }
      footerButtons={{
        onClick: onSave,
        onCancel: onClose,
        isDisable: !sheetDataHasChanges,
      }}
    />
  );
}

ErrorsModal.propTypes = errorsModalPropTypes;

export default ErrorsModal;
