import React from "react";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listMarketRooms,
  craeteMarketRoomCategory,
  updateMarketRoomCategory,
  deleteMarketRoomCategory,
  reorderCategories,
} from "~/modules/signed/modules/cms/modules/admin/store/marketRooms/actions";

//* APIs
import { reorderCategory } from "~/modules/signed/modules/cms/modules/admin/apis/categories";

//* CUSTOM IMPORT
import { Icon, Typography } from "~/shared/components/atoms";
import CategoryModal from "~/modules/signed/modules/cms/components/modals/CategoryModal";
import { CATEGORY_TYPES } from "~/shared/utils/types";
import {
  DraggableList,
  Empty,
  LoaderContainer,
} from "~/shared/components/molecules";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import {
  AddCategoryButton,
  MarketRoom,
  Header,
  Partners,
  IconButton,
} from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function MarketRooms() {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: categories, isLoaded } = useSelector(
    state => state.adminMarketRooms.list
  );

  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  //* STATES
  const [showModal, setShowModal] = React.useState(false);
  const [categoryData, setCategoryData] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listMarketRooms());

    return () => {
      isMounted = false;
    };
  }, [dispatch]);

  const openModal = () => setShowModal(true);

  const closeModal = () => setShowModal(false);

  const onCreateCategory = response =>
    dispatch(craeteMarketRoomCategory(response.data));

  const onUpdateCategory = response => {
    const category = response.data;

    dispatch(updateMarketRoomCategory(category, category.id));
  };

  const onDeleteCategory = () =>
    dispatch(deleteMarketRoomCategory(Number(categoryData.id)));

  const handleCreateCategories = () => {
    setCategoryData(null);
    openModal();
  };

  async function handleSortList(sortedList) {
    const reorderedList = sortedList.map((field, index) => {
      return {
        ...field,
        id: Number(field.id),
        order: index,
      };
    });
    dispatch(reorderCategories({ reorderedList }));
    await reorderCategory({ reorderedList });
  }

  const renderCell = category => (
    <MarketRoom key={category.id}>
      <Header>
        <Icon iconName="drag" />

        <Typography
          className="subTitle accent bold"
          style={{
            margin: "0 8px 4px",
          }}
        >
          {dTranslate(category.name)}
        </Typography>

        <IconButton
          title={translate("entities.broadcastRooms.iconButtonTitle")}
          onClick={() => {
            setCategoryData(category);
            openModal();
          }}
        >
          <Icon iconName="edit" size={16} variant="accent" />
        </IconButton>
      </Header>

      <Partners
        isLoaded={isLoaded}
        partners={category.partners}
        marketRoomName={dTranslate(category.name)}
        marketRoomId={Number(category.id)}
        level={category.level}
      />
    </MarketRoom>
  );

  return (
    <>
      {isLoaded ? (
        <>
          {categories.length === 0 ? (
            <Empty
              style={{ position: "relative", bottom: 100 }}
              icon={<Icon iconName="marketRooms" />}
              message={translate(
                "entities.marketRooms.emptyCategories.message"
              )}
              description={translate(
                "entities.marketRooms.emptyCategories.description"
              )}
              button={{
                onClick: handleCreateCategories,
                children: translate("entities.categories.addCategories"),
                icon: <Icon variant="white" iconName="add" />,
              }}
            />
          ) : (
            <>
              <AddCategoryButton type="button" onClick={handleCreateCategories}>
                {translate("entities.categories.addCategories")}
              </AddCategoryButton>

              <DraggableList
                isDragDisabled={categories.length === 1}
                data={categories.map(category => ({
                  ...category,
                  id: String(category.id),
                }))}
                renderCell={renderCell}
                onDragFinish={handleSortList}
              />
            </>
          )}
        </>
      ) : (
        <LoaderContainer size={9} gap={20} cellLoader={<CellLoader />} />
      )}

      <CategoryModal
        show={showModal}
        type={CATEGORY_TYPES.MARKET_ROOM}
        orderLimit={categories.length}
        onClose={closeModal}
        onCreate={onCreateCategory}
        onUpdate={onUpdateCategory}
        onDelete={onDeleteCategory}
        category={categoryData}
      />
    </>
  );
}

export default MarketRooms;
