import React from "react";

// APIs
import {
  listEvents,
  createEvent,
  updateEvent,
  deleteEvent,
} from "~/modules/signed/modules/cms/modules/admin/apis/events";

// CUSTOM IMPORTS
import { createDate, getFullDate, getFullHours } from "~/shared/utils/dates";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { SimpleCell } from "~/shared/components/molecules";
import { CRUDModal } from "~/shared/components/organisms";
import { Typography } from "~/shared/components/atoms";
import { Container, EventList, EventForm, AddButton } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function EventFragment() {
  // STATE
  const [events, setEvents] = React.useState([]);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isFormVisible, setIsFormVisible] = React.useState(false);
  const [event, setEvent] = React.useState({});
  const [isCreateForm, setIsCreateForm] = React.useState(false);
  const [modal, setModal] = React.useState({
    isVisible: false,
    type: null,
  });
  const [isCreating, setIsCreating] = React.useState(false);
  const [isUpdating, setIsUpdating] = React.useState(false);
  const [isDeleting, setIsDeleting] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const fetch = async () => {
      await listEvents({ setIsLoaded, setEvents });
    };

    fetch();
  }, [setEvents]);

  function onCellClick(e = {}) {
    setIsFormVisible(false);

    if (e === undefined) {
      setEvent(e);
    } else {
      const { event_start_at } = e;

      setEvent({
        ...e,
        initialDate: getFullDate(event_start_at),
        initialTime: getFullHours(event_start_at),
      });
    }

    setTimeout(() => setIsFormVisible(true), 10);
  }

  function onFormSubmit(data) {
    const { initialDate, initialTime } = data;

    const eventStart = createDate({ date: initialDate, time: initialTime });

    setEvent({ ...event, ...data, event_start_at: eventStart });
    setModal({ isVisible: true, type: !isCreateForm ? "update" : "create" });
  }

  async function onModalConfirm() {
    setIsFormVisible(false);

    if (isCreateForm) {
      await createEvent({
        eventData: event,
        events,
        setEvents,
        setIsCreating,
      }).then(() => setEvent({}));

      setEvent({});
    } else {
      await updateEvent({
        eventData: event,
        eventId: event.id,
        events,
        setEvents,
        setIsUpdating,
      }).then(() => setEvent({}));
    }

    setModal({ isVisible: false, type: null });
  }

  function onCellDelete(event) {
    setEvent(event);

    deleteEvent({ eventId: event.id, setIsDeleting, events, setEvents });
  }

  const renderCell = cell => {
    const { event_id: eventId, title } = cell;

    const handleEdit = () => {
      onCellClick(cell);
      setIsCreateForm(false);
    };

    return (
      <SimpleCell
        varaint={cell.id === event.id && "accent"}
        onClick={handleEdit}
        onEdit={handleEdit}
        onDelete={onCellDelete}
        entity={translate("entities.events.singular")}
        entityName={title}
      >
        <Typography className="subTitle bold" maxLines={2}>
          {title}
        </Typography>

        <Typography className="opaque" maxLines={3}>
          {`${translate("entities.events.id")}: ${eventId}`}
        </Typography>
      </SimpleCell>
    );
  };

  return (
    <Container>
      <EventList
        cellId={event && event.id}
        isUpdating={isUpdating}
        isDeleting={isDeleting}
        loaderListSize={5}
        withScroll
        firstCell={
          <AddButton
            isActive={isCreateForm}
            placeholder={translate("entities.events.add")}
            direction="row"
            onClick={() => {
              onCellClick();
              setIsCreateForm(true);
            }}
          />
        }
        orientation="column"
        isLoaded={isLoaded}
        data={events}
        renderCell={renderCell}
        renderCellLoader={() => <CellLoader height={74} width={400} />}
        cellStyle={{ marginBottom: 10, maxWidth: "100%" }}
      />

      {isFormVisible && (
        <EventForm
          onSubmit={onFormSubmit}
          event={event}
          onCancel={() => {
            setIsFormVisible(false);
            setIsCreateForm(false);
            setEvent({});
          }}
          onCheckbox={isChecked => {
            setEvent({ ...event, send_email: isChecked });
          }}
        />
      )}

      <CRUDModal
        show={modal.isVisible}
        type={modal.type}
        entity={translate("entities.events.singular")}
        entityName={event.title}
        onClose={() => setModal({ isVisible: false })}
        onConfirm={onModalConfirm}
        isLoading={isCreating || isUpdating}
      />
    </Container>
  );
}

export default EventFragment;
