import styled, { css } from "styled-components";
import Button from "@material-ui/core/Button";

export const Container = styled.div`
  position: relative;

  &:hover {
    .button-tip {
      opacity: 0.5;
      visibility: visible;
    }
  }

  .MuiTouchRipple-root {
    color: ${props =>
      props.rippleColor ? props.rippleColor : props.theme.colors.accent};
  }

  .MuiButtonBase-root {
    padding: 16px;
    border-radius: 8px;
    background-color: ${props => props.backgroundColor};
    min-width: unset !important;

    &:hover {
      background-color: ${props => props.hoverColor};
    }

    ${props =>
      props.size === "small" &&
      css`
        height: 40px;

        .MuiButton-label {
          font-size: 12px;
        }
      `}
  }
`;

export const MaterialButton = styled(Button)`
  height: 100%;
  width: 100%;

  .MuiButton-label {
    font: 14px "Lato", sans-serif;
    text-transform: none;
    font-weight: bold;

    svg {
      color: ${props => props.fontColor};

      path {
        fill: ${props => props.fontColor};
      }
    }

    color: ${props => props.fontColor};

    .MuiButton-startIcon {
      margin-right: ${props => (props.children ? "10px" : 0)};
    }
  }
`;

export const Icon = styled.div`
  margin-right: ${props => (props.hasChildren ? "10px" : 0)};

  svg {
    min-height: 20px;
    min-width: 20px;
  }
`;

export const TextContainer = styled.p`
  margin: 0;
`;
