import {
  getHours,
  getMinutes,
  getDate,
  getMonth,
  getYear,
  differenceInHours,
  differenceInMinutes,
  differenceInSeconds,
  formatDistanceToNow,
  parseISO,
  isBefore,
  isAfter,
  isToday as isTodayDateFns,
} from "date-fns";
import locale from "date-fns/locale/pt-BR";
import i18n from "../../translation/i18n";

//* CUSTOM IMPORTS
import { isDataValid } from "../validations";

export const TIME_COURSE = {
  MORNING: "morning",
  EVENING: "evening",
  NIGHT: "night",
  DAWN: "danw",
};

function getMounthName(mounthNumber, abbreviated = false) {
  const monthNames = [
    i18n.t("common.january"),
    i18n.t("common.february"),
    i18n.t("common.march"),
    i18n.t("common.april"),
    i18n.t("common.may"),
    i18n.t("common.june"),
    i18n.t("common.july"),
    i18n.t("common.august"),
    i18n.t("common.september"),
    i18n.t("common.october"),
    i18n.t("common.november"),
    i18n.t("common.december"),
  ];

  if (abbreviated) return monthNames[mounthNumber].substring(0, 3);

  return monthNames[mounthNumber];
}

export function getFullDateBR(date) {
  const formmatedDate = new Date(date);

  const year = getYear(formmatedDate);
  const mounth = getMonth(formmatedDate) + 1;
  const day = getDate(formmatedDate);

  let formmattedDay = day;
  let formmattedMounth = mounth;

  // Add a left 0 if data has no decimal house
  if (day <= 9) formmattedDay = `0${day}`;
  if (mounth <= 9) formmattedMounth = `0${mounth}`;

  return `${formmattedDay}/${formmattedMounth}/${year}`;
}

export function getFullDate(
  date,
  suffix = false,
  abbreviated = false,
  withoutYear = false
) {
  if (!isDataValid(date)) return null;

  const formmatedDate = new Date(date);

  const year = getYear(formmatedDate);
  const mounth = getMonth(formmatedDate) + 1;
  const day = getDate(formmatedDate);

  let formmattedDay = day;
  let formmattedMounth = mounth;
  let formattedYear = ` ${year}`;

  // Add a left 0 if data has no decimal house
  if (day <= 9) formmattedDay = `0${day}`;
  if (mounth <= 9) formmattedMounth = `0${mounth}`;

  let result;

  //* Remove year
  if (withoutYear) formattedYear = "";

  if (suffix) {
    result = `${formmattedDay}, ${getMounthName(
      mounth - 1,
      abbreviated
    )} ${formattedYear}`;
  } else {
    result = `${year}-${formmattedMounth}-${formmattedDay}`;
  }

  return result;
}

export function getFullHours(date) {
  if (date) {
    const parsedDate = new Date(date);

    const hours = getHours(parsedDate);
    const minutes = getMinutes(parsedDate);

    let formmattedMinutes = minutes;
    let formmattedHours = hours;

    // Add a left 0 if data has no decimal house
    if (minutes <= 9) formmattedMinutes = `0${minutes}`;
    if (hours <= 9) formmattedHours = `0${hours}`;

    return `${formmattedHours}:${formmattedMinutes}`;
  }
  return null;
}

// Return the minutes between 2 dates
export const getMinutesBetweenDates = (startDate, endDate) => {
  const startTime = new Date(startDate);
  const endTime = new Date(endDate);

  const minutes = differenceInMinutes(endTime, startTime);

  const seconds = differenceInSeconds(endTime, startTime);

  const result = `${minutes}.${seconds}`;

  return Number(result);
};

// Return difference betwen 2 dates
export const getFullHoursBetweenDates = (
  startDate,
  endDate,
  suffix = false
) => {
  const startTime = new Date(startDate);
  const endTime = new Date(endDate);

  const hours = differenceInHours(endTime, startTime);

  const minutes = differenceInMinutes(endTime, startTime);

  const formattedMinutes = minutes - hours * 60;

  // If suffix is true return response as String
  if (suffix) {
    if (hours < 0) return `0h`;

    if (formattedMinutes > 0) return `${hours}h e ${formattedMinutes}min`;

    return `${hours}h`;
  }

  if (hours < 0) return { hours: 0, minutes: 0 };

  if (formattedMinutes > 0) return { hours, minutes: formattedMinutes };

  return { hours, minutes: 0 };
};

export const getDistanceToNow = date => {
  let parsedDate = parseISO(date);

  if (date instanceof Date) parsedDate = date;

  const result = formatDistanceToNow(parsedDate, {
    addSuffix: true,
    locale,
  });

  return result;
};

export const createDate = ({ date, time }) => new Date(`${date}T${time}`);

export function numberToTime(number) {
  const hours = number / 60;
  const rhours = Math.floor(hours);
  const minutes = (hours - rhours) * 60;
  const rminutes = Math.round(minutes);

  if (rminutes <= 0) return `${rhours}h`;

  return `${rhours}h e ${rminutes}min`;
}

/** This function will convert minutes and return the hour and minutes. */
export const minutesToTime = numberMinutes => {
  const hours = numberMinutes / 60;
  const rhours = Math.floor(hours);
  const minutes = (hours - rhours) * 60;
  const rminutes = Math.round(minutes);

  return { hours: rhours, minutes: rminutes };
};

export const isSameDay = ({ dateLeft, dateRight }) =>
  new Date(dateLeft).toLocaleDateString() ===
  new Date(dateRight).toLocaleDateString();

export function isDateBefore({ dateToCompare, date }) {
  const date1 = new Date(dateToCompare);
  const date2 = new Date(date);

  return isBefore(date1, date2);
}

export function isDateAfter({ dateToCompare, date }) {
  const date1 = new Date(dateToCompare);
  const date2 = new Date(date);

  return isAfter(date1, date2);
}

/** This function will ignore day in date, only compare hours beetween 2 dates */
export const isHourBefore = (date, hourToCompare) =>
  isDateBefore({
    date: `2021-09-07T${hourToCompare}:00.000Z`,
    dateToCompare: `2021-09-07T${getFullHours(date)}:00.000Z`,
  });

/** This function will check if an hour of date is
 * beetween an specific range of hours.
 */
export const isHourBetween = (date, startHour, endHour) => {
  return !isHourBefore(date, startHour) && isHourBefore(date, endHour);
};

/** This function will check if an date is in morning period */
export const isMorning = date => isHourBetween(date, "06:00", "12:00");

/** This function will check if an date is in evening period */
export const isEvening = date => isHourBetween(date, "12:00", "18:00");

/** This function will check if an date is in night period */
export const isNight = date => isHourBetween(date, "18:00", "23:59");

/** This function will check if an date is in DAWN period */
export const isDawn = date => isHourBetween(date, "00:00", "06:00");

/** This function will return the timeCourse of an specific date. */
export const getTimeCourse = date => {
  const { DAWN, EVENING, MORNING, NIGHT } = TIME_COURSE;

  if (isMorning(date)) return MORNING;

  if (isEvening(date)) return EVENING;

  if (isNight(date)) return NIGHT;

  if (isDawn(date)) return DAWN;
};

export function getTimezone({ dateTimeZone }) {
  const date = new Date(dateTimeZone);

  const timeZone = date.getTimezoneOffset();

  if (timeZone === 0) return 0;

  const timeZoneFormated = timeZone / 60;

  return timeZoneFormated;
}

export function getCurrentDay({ subtractDays = 0, inMidnigth = false }) {
  const date = new Date();

  //* Subtract days of the current date
  if (subtractDays > 0) date.setDate(date.getDate() - subtractDays);

  //* Set the day time to midnigth
  if (inMidnigth) date.setHours(0, 0, 0, 0);

  return date;
}

export const isToday = ({ date }) => {
  let parsedDate = parseISO(date);

  if (date instanceof Date) parsedDate = date;

  return isTodayDateFns(parsedDate);
};
