import React from "react";
import PropTypes from "prop-types";
import { useParams, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  showUser,
  clearUser,
} from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

//* CUSTOM IMPORTS
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminUsers } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { Container, UserInfos, UserActivities, SwipeTabs } from "./styles";

import ModerationTab from "./components/ModerationTab";
import GroupsTab from "./components/GroupsTab";

import { useTranslation } from "~/shared/hooks/translation";
import i18n from "~/shared/translation/i18n";

//* TABS
const tabs = [
  { id: 1, name: i18n.t("entities.tabs.userDetailsTabsOne") },
  { id: 2, name: i18n.t("entities.tabs.userDetailsTabsTwo") },
  { id: 3, name: i18n.t("entities.tabs.userDetailsTabsThree") },
];

function UserDetails() {
  const { translate } = useTranslation();

  const { userId } = useParams();
  const location = useLocation();

  const dispatch = useDispatch();
  const { data: user, isLoaded } = useSelector(
    state => state.adminUsers.object
  );

  //* FUNCTIONS
  const { setBreadCrumbs, setEditMode } = useCMS();

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.users.plural"),
        function: openAdminUsers,
      },
      {
        id: 2,
        name: isDataValid(location.state) && location.state.userName,
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [location.state, setBreadCrumbs, setEditMode, translate]);

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted && isDataValid(userId)) dispatch(showUser(userId));

    return () => {
      dispatch(clearUser());
      isMounted = false;
    };
  }, [dispatch, userId]);

  return (
    <Container id="user-details-container">
      <UserInfos userId={userId} user={user} isLoaded={isLoaded} />

      <SwipeTabs size="small" alignment="left" tabs={tabs}>
        <UserActivities userId={userId} />

        {isLoaded && <GroupsTab user={user} />}

        {isLoaded && <ModerationTab user={user} />}
      </SwipeTabs>
    </Container>
  );
}

UserDetails.propTypes = {
  userId: PropTypes.number.isRequired,
};

export default UserDetails;
