import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import SpeakerCell from "../../cells/SpeakerCell";
import { isListValid } from "../../../../utils/validations";
import SpeakerDetailsModal from "../../../../../modules/signed/components/speakers/SpeakerDetailsModal";

function SpeakerViewList({ className, speakers, renderCustomButton }) {
  // STATES
  const [showModal, setShowModal] = React.useState(false);
  const [speaker, setSpeaker] = React.useState(null);

  return (
    isListValid(speakers) && (
      <div className={className}>
        {speakers.map(s => (
          <SpeakerCell
            key={s.id}
            speaker={s}
            onClick={() => {
              setSpeaker(s);
              setShowModal(true);
            }}
          />
        ))}

        {speaker && (
          <SpeakerDetailsModal
            speaker={speaker}
            show={showModal}
            customButton={renderCustomButton(speaker)}
            onClose={() => setShowModal(false)}
          />
        )}
      </div>
    )
  );
}

SpeakerViewList.propTypes = {
  speakers: PropTypes.arrayOf(PropTypes.object).isRequired,
  className: PropTypes.string,
  renderCustomButton: PropTypes.func,
};

SpeakerViewList.defaultProps = {
  className: "speaker-view-list",
  renderCustomButton: () => {},
};

export default SpeakerViewList;
