import React from "react";
import PropTypes from "prop-types";
import { MdMoreVert } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { ThemeContext } from "styled-components";

// ACTIONS
import { deleteUser } from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import { Container, Scroll, Card, Options } from "./styles";
import { LabeledAvatarInput, DialogModal } from "~/shared/components/molecules";
import UserAttributes from "~/modules/signed/modules/cms/components/lists/UserAttributeList";

import { openAdminUsers, openEditUser } from "~/shared/routes/navigation";

import { useTranslation } from "~/shared/hooks/translation";

function UserInfos({ className, user, userId, isLoaded }) {
  const { translate } = useTranslation();

  const { colors } = React.useContext(ThemeContext);
  const dispatch = useDispatch();
  const { isDeleting } = useSelector(state => state.adminUsers.list);

  // STATES
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  // FUNCTIONS
  const fillItems = () => {
    const items = [
      {
        id: 1,
        name: translate("entities.users.deleteUser.nameEditButton"),
        function: () => openEditUser({ userId }),
      },
      {
        id: 2,
        name: translate("entities.users.deleteUser.nameDeleteButton"),
        function: () => setShowDeleteModal(true),
        color: colors.redWrong,
      },
    ];

    return items;
  };

  React.useMemo(() => {
    if (isDeleting === "FINISH") {
      setShowDeleteModal(false);
      openAdminUsers();
    }
  }, [isDeleting]);

  return (
    <>
      <Container className={className}>
        <div id="user-info-avatar-area">
          <LabeledAvatarInput
            disabled
            isLoaded={isLoaded}
            src={user.avatar}
            noRegistration
            labelMessage={
              <>
                <strong>{user.name}</strong>
                <h4>{user.email}</h4>
              </>
            }
          />
          <Options buttonIcon={<MdMoreVert />} fillItems={fillItems} />
        </div>

        <Card>
          <Scroll>
            <UserAttributes user={user} isUserLoaded={isLoaded} />
          </Scroll>
        </Card>
      </Container>

      <DialogModal
        show={showDeleteModal}
        title={translate("entities.users.deleteUser.title")} // Excluir usuário
        description={`${translate("entities.users.deleteUser.description")} ${
          user.name
        } ?`}
        // Tem certeza que deseja excluir o usuário
        onCancel={() => setShowDeleteModal(false)}
        onClose={() => setShowDeleteModal(false)}
        onConfirm={() => dispatch(deleteUser(userId))}
        isLoading={isDeleting === "PENDING"}
      />
    </>
  );
}

UserInfos.propTypes = {
  className: PropTypes.string,
  userId: PropTypes.number.isRequired,
  user: PropTypes.object.isRequired,
  isLoaded: PropTypes.bool,
};

UserInfos.defaultProps = {
  className: null,
  isLoaded: false,
};

export default UserInfos;
