import types from "./types";

// CATEGORIES
export function receiveCategories({ broadcastRoomCategories }) {
  return {
    type: types.CATEGORIES.RECEIVE,
    payload: { broadcastRoomCategories },
  };
}

// REORDER CATEGORIES
export function reorderCategories({ reorderedList }) {
  return {
    type: types.CATEGORIES.REORDER,
    payload: { reorderedList },
  };
}

export function addCategory({ newCategory }) {
  return {
    type: types.CATEGORIES.ADD,
    payload: { newCategory },
  };
}
export function changeCategory({ updatedCategory }) {
  return {
    type: types.CATEGORIES.CHANGE,
    payload: { updatedCategory },
  };
}
export function removeCategory({ categoryId }) {
  return {
    type: types.CATEGORIES.REMOVE,
    payload: { categoryId },
  };
}

// BROADCAST ROOMS
export function paginateBroadcastRoom({ broadcastRooms, categoryId }) {
  return {
    type: types.BROADCAST_ROOMS.PAGINATE,
    payload: { broadcastRooms, categoryId },
  };
}

export function removeBroadcastRoom({ broadcastRoomId, categoryId }) {
  return {
    type: types.BROADCAST_ROOMS.REMOVE,
    payload: { broadcastRoomId, categoryId },
  };
}

// SHOW
export function showBroadcastRoom(broadcastRoomId) {
  return {
    type: types.show.REQUEST,
    payload: { broadcastRoomId },
  };
}
export function showBroadcastRoomSuccessful(broadcastRoom) {
  return {
    type: types.show.SUCCESSFUL,
    payload: { broadcastRoom },
  };
}

// FORM
export function clearFormData() {
  return {
    type: types.form.CLEAR,
  };
}

export function setTagIds(tagIds) {
  return {
    type: types.form.SET_TAGS_IDS,
    payload: { tagIds },
  };
}

export function setGroupIds(groupIds) {
  return {
    type: types.form.SET_GROUP_IDS,
    payload: { groupIds },
  };
}

export function setPartnerIds(partnerIds) {
  return {
    type: types.form.SET_PARTNER_IDS,
    payload: { partnerIds },
  };
}

export function setScheduleIds(scheduleIds, scheduleId = null) {
  return {
    type: types.form.SET_SCHEDULE_IDS,
    payload: { scheduleIds, scheduleId },
  };
}
export function addScheduleId(scheduleId) {
  return {
    type: types.form.ADD_SCHEDULE_ID,
    payload: { scheduleId },
  };
}
export function removeScheduleId(scheduleId) {
  return {
    type: types.form.REMOVE_SCHEDULE_ID,
    payload: { scheduleId },
  };
}
