import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getBroadcastRoomsReportCountAccess } from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { DashboardAccessReportCard } from "~/shared/components/molecules";
import {
  openAdminBroadcastRooms,
  openAdminBroadcastRoomsAccess,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function BroadcastRoomsAccessCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: broadcastRooms, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getBroadcastRoomsReportCountAccess({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardAccessReportCard
      isLoaded={isLoaded}
      title={translate("entities.broadcastRooms.accessesPerRoom")}
      dotColor="#2BFD6A"
      accessReportItems={
        isLoaded &&
        broadcastRooms.data.map(broadcastRoom => ({
          ...broadcastRoom,
          title: dTranslate(broadcastRoom.title),
        }))
      }
      emptyMessage="Ainda não há salas cadastradas."
      onEmptyButtonClick={openAdminBroadcastRooms}
      emptyButtonPlaceholder="Criar sala"
      onLinkClick={() =>
        openAdminBroadcastRoomsAccess({
          endDate: dateInterval.endDate,
          startDate: dateInterval.startDate,
          timezone,
        })
      }
    />
  );
}

BroadcastRoomsAccessCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
  timezone: PropTypes.number.isRequired,
};

export default BroadcastRoomsAccessCard;
