import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { GamificationCard } from "./styles";

const pointsNotificationPropTypes = {
  notification: PropTypes.shape({
    description: PropTypes.string,
  }).isRequired,

  onAutoClose: PropTypes.func.isRequired,
};

const AUTO_CLOSE_INITIAL_VALUE = 3000;

const PointsNotification = ({ notification, onAutoClose, ...rest }) => {
  //* STATES
  const [animate, setAnimate] = React.useState(false);
  const [show, setShow] = React.useState(true);

  const animTime = 400;

  React.useEffect(() => {
    setTimeout(() => {
      setAnimate(true);
    }, AUTO_CLOSE_INITIAL_VALUE);

    setTimeout(() => {
      setShow(false);
      onAutoClose();
    }, AUTO_CLOSE_INITIAL_VALUE + animTime);
  }, [onAutoClose]);

  return (
    show && (
      <GamificationCard
        animate={animate}
        color="primary"
        icon={{ variant: "white", iconName: "trophy", size: 16 }}
        description={{
          children: notification.description,
          className: "bold white small",
        }}
        {...rest}
      />
    )
  );
};

PointsNotification.propTypes = pointsNotificationPropTypes;

export { PointsNotification };
