import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  deleteSponsorMeeting,
  listSponsorMeetings,
} from "../../../apis/meetings";

//* CUSTOM IMPORTS
import { meetingTypes } from "./store/types";
import { meetingsReducer, MEETINGS_INITIAL_DATA } from "./store/reducer";
import { useSponsorCMS } from "../../../hooks";

const SponsorMeetingsContext = React.createContext(null);

const SponsorMeetingsProvider = ({ children }) => {
  const [meetings, dispatch] = React.useReducer(
    meetingsReducer,
    MEETINGS_INITIAL_DATA
  );

  const { managedPartnerId } = useSponsorCMS();

  //* FUNCTIONS
  const listMeetings = React.useCallback(
    search => {
      if (managedPartnerId) {
        dispatch({ type: meetingTypes.LIST.REQUEST });

        listSponsorMeetings({
          page: 1,
          search,
          partnerId: managedPartnerId,
        }).then(({ data }) =>
          dispatch({ type: meetingTypes.LIST.SUCCESS, payload: data })
        );
      }
    },
    [managedPartnerId]
  );

  /** This function will paginate meetings states and deal with all sideEffects */
  const paginateMeetings = React.useCallback((page, search) => {
    dispatch({ type: meetingTypes.PAGINATE.REQUEST });

    listSponsorMeetings({ page, search }).then(({ data }) =>
      dispatch({ type: meetingTypes.PAGINATE.SUCCESS, payload: data })
    );
  }, []);

  /** This function will call an APi to cancel the meetingm, then deals with sideEffect */
  const cancelMyMeeting = (scheduledMeetingId, startDate) => {
    dispatch({
      type: meetingTypes.DELETE.REQUEST,
      payload: { scheduledMeetingId, startDate },
    });

    deleteSponsorMeeting({ scheduledMeetingId }).then(() => {
      dispatch({
        type: meetingTypes.DELETE.SUCCESS,
        payload: { scheduledMeetingId, startDate },
      });
    });
  };

  /** This function only add an newMetting in meetings.data list. */
  const addMeeting = React.useCallback(newMeeting => {
    dispatch({ type: meetingTypes.ADD.SUCCESS, payload: { newMeeting } });
  }, []);

  return (
    <SponsorMeetingsContext.Provider
      value={{
        //* States
        meetings,

        //* Functions
        addMeeting,
        listMeetings,
        cancelMyMeeting,
        paginateMeetings,
      }}
    >
      {children}
    </SponsorMeetingsContext.Provider>
  );
};

SponsorMeetingsProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useSponsorMeetings = () => {
  const context = React.useContext(SponsorMeetingsContext);

  if (!context)
    throw new Error(
      "useSponsorMeetings must be within SponsorMeetingsProvider"
    );

  return context;
};

export { useSponsorMeetings, SponsorMeetingsProvider };
