import React from "react";

// CUSTOM IMPORTS
import { INPUT_TYPES } from "~/shared/utils/inputs";
import StatesDropdown from "./components/StatesDropdown";
import CitiesDropdown from "./components/CitiesDropdown";
import AttributesDropdown from "./components/AttributesDropdown";

function DynamicDropdown({ inputType, dropdownOptions, refCode, ...rest }) {
  function renderDropdown() {
    switch (inputType) {
      case INPUT_TYPES.STATE.value:
        return <StatesDropdown {...rest} />;

      case INPUT_TYPES.CITY.value:
        return <CitiesDropdown {...rest} />;

      case INPUT_TYPES.SELECT.value:
        return (
          <AttributesDropdown
            initialData={dropdownOptions}
            refCode={refCode}
            {...rest}
          />
        );

      default:
        return <></>;
    }
  }

  return renderDropdown();
}

export { DynamicDropdown };
