import React from "react";
import { Form } from "@unform/web";
import * as Yup from "yup";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  isSameDay,
  isDateBefore,
  getFullDate,
  getFullHours,
  createDate,
} from "../../../../utils/dates";
import { isDataValid } from "../../../../utils/validations";
import { getYupErrors } from "../../../../utils/forms";
import { Modal } from "../../../molecules";
import { Button, Trans } from "../../../atoms";
import { Input } from "./styles";
import { useTranslation } from "../../../../hooks/translation";

function DateSelectModal({ show, onClose, onSubmit }) {
  const formRef = React.useRef(null);

  //* FUNCTIONS
  const { translate } = useTranslation();

  const handleSubmit = React.useCallback(
    async data => {
      try {
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        //* If some inputs are not filled use the nullableSchema
        if (
          data.endDay === "" ||
          data.startDay === "" ||
          data.startTime === "" ||
          data.endTime === ""
        ) {
          const nullableSchema = Yup.object()
            .shape({
              startDay: Yup.string().required(
                translate("handlers.startDate.isRequired")
              ),
              endDay: Yup.string().required(
                translate("handlers.endDate.isRequired")
              ),
              startTime: Yup.string().required(
                translate("handlers.startTime.isRequired")
              ),
              endTime: Yup.string().required(
                translate("handlers.endTime.isRequired")
              ),
            })
            .nullable();

          await nullableSchema.validate(data, { abortEarly: false });
        } else {
          const startDate = createDate({
            date: data.startDay,
            time: data.startTime,
          });
          const endDate = createDate({ date: data.endDay, time: data.endTime });

          const schemaShape = {
            startDay: Yup.date(),
            endDay: Yup.date()
              .default(null)
              .when(
                "startDay",
                (started, yup) =>
                  started &&
                  yup.min(
                    started,
                    translate(
                      "handlers.endDate.isNotEqualsOrGreaterThanStartDate"
                    )
                  )
              ),
          };

          if (isSameDay({ dateLeft: startDate, dateRight: endDate })) {
            schemaShape.endTime = Yup.string().test(
              "is endTime lesser than startTime",
              translate("handlers.endTime.isNotGreaterThanStartTime"),
              () =>
                isDateBefore({
                  date: endDate,
                  dateToCompare: startDate,
                })
            );
          }

          const schema = Yup.object().shape(schemaShape);

          //* Convert input data to date format
          const formmatedData = {
            startDay: startDate,
            endDay: endDate,
            ...data,
          };

          await schema.validate(formmatedData, { abortEarly: false });
        }

        onSubmit(
          createDate({ date: data.startDay, time: data.startTime }),
          createDate({ date: data.endDay, time: data.endTime })
        );

        onClose();
      } catch (err) {
        const errors = getYupErrors(err);

        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [onClose, onSubmit, translate]
  );

  const setInputDateMaxValue = () => getFullDate(new Date());

  const handleDateInputChange = (e, inputId) => {
    const { value } = e.target;
    const timeInput = document.getElementById(inputId);

    const [year, mounth, day] = String(value).split("-");
    const today = new Date();
    const todayYear = today.getFullYear();
    const todayMounth = today.getUTCMonth() + 1;
    const todayDay = today.getUTCDate();

    let formmatedDay = todayDay;
    let formattedMounth = todayMounth;

    if (todayDay < 10) formmatedDay = `0${todayDay}`;

    if (todayMounth < 10) formattedMounth = `0${todayMounth}`;

    //* If data is today, add a max value into time input
    if (
      year === String(todayYear) &&
      mounth === String(formattedMounth) &&
      day === String(formmatedDay)
    ) {
      timeInput.setAttribute("max", getFullHours(new Date()));
    } else {
      timeInput.setAttribute("max", null);
    }

    return null;
  };

  return (
    <Modal
      title={translate("components.dateSelect.filterModal.title")}
      show={show}
      onClose={onClose}
    >
      <Form onSubmit={handleSubmit} ref={formRef}>
        <fieldset style={{ display: "flex" }}>
          <Input
            title={translate("components.input.startDate.title")}
            name="startDay"
            type="date"
            max={setInputDateMaxValue()}
            onChange={e => handleDateInputChange(e, "startTime")}
          />
          <Input
            title={translate("components.input.startTime.title")}
            id="startTime"
            name="startTime"
            type="time"
            style={{ marginLeft: 20 }}
          />
        </fieldset>

        <fieldset style={{ display: "flex", marginTop: 40 }}>
          <Input
            title={translate("components.input.endDate.title")}
            name="endDay"
            type="date"
            max={setInputDateMaxValue()}
            onChange={e => handleDateInputChange(e, "endTime")}
          />
          <Input
            title={translate("components.input.endTime.title")}
            name="endTime"
            id="endTime"
            type="time"
            style={{ marginLeft: 20 }}
          />
        </fieldset>

        <Button loading={false} type="submit" style={{ marginTop: 30 }}>
          <Trans translationKey="components.dateSelect.filterModal.submitButton">
            Filtrar
          </Trans>
        </Button>
      </Form>
    </Modal>
  );
}

DateSelectModal.propTypes = {
  show: PropTypes.bool,
  onClose: PropTypes.func,
  onSubmit: PropTypes.func,
};

DateSelectModal.defaultProps = {
  show: false,
  onClose: () => {},
  onSubmit: () => {},
};

export default DateSelectModal;
