import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listGroups,
  clearGroups,
  deleteGroup,
} from "~/modules/signed/modules/cms/modules/admin/store/groups/actions";

//* CUSTOM IMPORTS
import { Icon } from "~/shared/components/atoms";
import { openGroupModal } from "~/shared/routes/navigation";
import { CheckList, CRUDModal } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function GroupList({
  selectedGroups,
  setSelectedGroups,
  title,
  onSelect,
  ...rest
}) {
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  //* REDUCER
  const dispatch = useDispatch();
  const { data: groups, isLoaded, isDeleting } = useSelector(
    state => state.adminGroups.list
  );

  //* STATES
  const [groupData, setGroupData] = React.useState(null);
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listGroups());

    return () => {
      dispatch(clearGroups());
      isMounted = false;
    };
  }, [dispatch]);

  function onAddButton() {
    openGroupModal();
  }

  const getGroupsByGroupIds = groupIds => {
    const completeGroups = [];

    groupIds.forEach(id => {
      const group = groups.find(g => g.id === id);

      if (group) completeGroups.push(group);
    });

    return completeGroups;
  };

  function handleSelectGroup(groupId) {
    // Find the index of id
    const alreadySelected = selectedGroups.findIndex(item => item === groupId);

    // If do not have de id inside array will return - 1
    if (alreadySelected >= 0) {
      // Receive all the array less the id
      const filteredGroupIds = selectedGroups.filter(item => item !== groupId);

      setSelectedGroups(filteredGroupIds);
      onSelect(filteredGroupIds, getGroupsByGroupIds(filteredGroupIds));
    } else {
      const newGroupIds = [...selectedGroups, groupId];

      setSelectedGroups(newGroupIds);
      onSelect(newGroupIds, getGroupsByGroupIds(newGroupIds));
    }
  }

  const openDeleteModal = group => {
    setGroupData(group);
    setShowDeleteModal(true);
  };

  const closeDeleteModal = () => {
    setShowDeleteModal(false);
  };

  const handleOnDeleteGroup = () => {
    dispatch(deleteGroup(groupData.id));
    closeDeleteModal();
  };

  return (
    <>
      <CheckList
        {...rest}
        data={groups.map(group => ({
          ...group,
          groupTitle: group.title,
          title: dTranslate(group.title),
          isChecked: !!selectedGroups.includes(group.id),
        }))}
        isLoading={!isLoaded}
        onCheck={group => handleSelectGroup(group.id)}
        onClick={group => handleSelectGroup(group.id)}
        onEdit={group => openGroupModal({ ...group, title: group.groupTitle })}
        onDelete={group => openDeleteModal(group)}
        title={title}
        iconedButton={{
          children: translate("entities.groups.placeholderAddGroup"),
          onClick: onAddButton,
        }}
        scrollStyle={{ maxHeight: 350 }}
        empty={{
          description: translate("entities.groups.empty.description"),
          message: translate("entities.groups.empty.message"),
          icon: <Icon iconName="networkRooms" />,
        }}
        cellList={{
          cellId: groupData && groupData.id,
          isDeleting: isDeleting === "PENDING",
        }}
      />

      <CRUDModal
        type="delete"
        show={showDeleteModal}
        entity={translate("common.group")}
        onClose={closeDeleteModal}
        onConfirm={handleOnDeleteGroup}
        isLoading={isDeleting === "PENDING"}
        entityName={groupData && dTranslate(groupData.title)}
      />
    </>
  );
}

GroupList.propTypes = {
  className: PropTypes.string,
  selectedGroups: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedGroups: PropTypes.func,
  title: PropTypes.string,
  onSelect: PropTypes.func,
};

GroupList.defaultProps = {
  className: "",
  title: null,
  onSelect: () => {},
  setSelectedGroups: () => {},
};

export default GroupList;
