import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useField } from "@unform/core";
import { Icon } from "../../atoms";
import { InputContainer, Input, Button } from "./styles";

const NumericInputProps = {
  /* Render an Title in input above */
  title: PropTypes.string,
  /** Callback when NumericInput changes value. Return the new value */
  onChange: PropTypes.number.isRequired,
  /* Tip is information regarding the title  */
  tip: PropTypes.string,
  /** Control the tip position */
  tipPosition: PropTypes.string,
  /** contorl the width tip in <b>px</b> */
  sizeTip: PropTypes.string,
  /** If you need to control the value more especific, you can pass an value props */
  value: PropTypes.number,
  /** Define the number <b>min</b> of this input value can reach */
  min: PropTypes.number,
  /** Define the number <b>max</b> of this input value can reach */
  max: PropTypes.Number,
  /** Name is required to reference the input inside the form  */
  name: PropTypes.string.isRequired,
  /** If <b>true</b> add an marker to represent that this input is required */
  isRequired: PropTypes.bool,
};

const NumericInputDefaultProps = {
  value: null,
  title: "",
  tip: "",
  tipPosition: "top",
  sizeTip: 200,
  min: 0,
  max: null,
  isRequired: false,
};

const NumericInput = ({
  value,
  onChange,
  title,
  tip,
  tipPosition,
  sizeTip,
  min,
  max,
  name,
  isRequired,
  ...rest
}) => {
  const inputRef = React.useRef(null);
  const { fieldName, defaultValue, registerField } = useField(name);

  const isMin = () => {
    if (inputRef.current) {
      const { value: inputValue } = inputRef.current;

      if (inputValue <= min) return true;
    }

    return false;
  };

  const isMax = () => {
    if (inputRef.current) {
      const { value: inputValue } = inputRef.current;

      if (max) if (inputValue >= max) return true;
    }

    return false;
  };

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, inputRef, registerField]);

  const handleInputValue = increment => {
    if (inputRef.current) {
      const { value: inputValue } = inputRef.current;

      inputRef.current.value = Number(inputValue) + increment;
    }
  };

  const handleOnAdd = () => !isMax() && handleInputValue(1);

  const handleOnMinus = () => !isMin() && handleInputValue(-1);

  return (
    <InputContainer
      {...rest}
      title={title}
      isRequired={isRequired}
      tip={tip}
      tipPosition={tipPosition}
      sizeTip={sizeTip}
    >
      <Input
        ref={inputRef}
        min={0}
        type="number"
        defaultValue={defaultValue || 0}
        value={value}
        onChange={onChange}
      />

      <Button variant="text" type="button" onClick={handleOnMinus}>
        <Icon iconName="minus" />
      </Button>

      <Button variant="text" type="button" onClick={handleOnAdd}>
        <Icon iconName="plus" />
      </Button>
    </InputContainer>
  );
};

NumericInput.propTypes = NumericInputProps;
NumericInput.defaultProps = NumericInputDefaultProps;

export { NumericInput, NumericInputProps };
