import produce from "immer";

const INITIAL_STATE = {
  sideMenuBar: {
    activeItemId: 1,
  },
};

export default function fluxReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // SET ACTIVE ITEM ID
      case "@adminFlux/SET_SIDE_MENU_BAR_ACTIVE_ITEM": {
        const { itemId } = action.payload;

        if (itemId) draft.sideMenuBar.activeItemId = itemId;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.sideMenuBar.activeItemId = 1;

        break;
      }
      default:
    }
  });
}
