import React, { createContext, useContext } from "react";
import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  createDownloadableMaterial,
  deleteDownloadableMaterial,
  getDownloadableMaterial,
  showDownloadableMaterial,
  updateDownloadableMaterial,
  getMaterialTotalCount,
  getMaterialUsersCountAccess,
} from "~/modules/signed/modules/cms/modules/admin/apis/downloadMaterials";
import { deleteItem } from "~/shared/utils/lists";
import { MATERIAL_TYPE } from "~/shared/utils/types";
import {
  openAdminDownloadMaterials,
  openFolderDetails,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const DownloadMaterialsContext = createContext(null);

const DownloadMaterialsProvider = ({ children }) => {
  const history = useHistory();
  const { translate } = useTranslation();
  const { toJsonTranslation } = useDynamicTranslation();

  // STATES
  const [materials, setMaterials] = React.useState([]);
  const [material, setMaterial] = React.useState(null);
  const [materialFolder, setMaterialFolder] = React.useState(null);
  const [materialTitle, setMaterialTitle] = React.useState(null);
  const [folderTitle, setFolderTitle] = React.useState(null);
  const [folders, setFolders] = React.useState([]);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const [materialTotalCount, setMaterialTotalCount] = React.useState(0);
  const [cardData, setCardData] = React.useState(null);
  const [tableData, setTableData] = React.useState(null);
  const [pagination, setPagination] = React.useState({
    page: 1,
    lastPage: 1,
  });
  const [selectedFolderPath, setSelectedFolderPath] = React.useState(0);
  const [folderPaths, setFolderPaths] = React.useState([]);
  const [copyList, setCopyList] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    const initialPath = [
      {
        id: 1,
        name: toJsonTranslation(
          translate("entities.downloadMaterials.downloadableMaterials")
        ),
        function: openAdminDownloadMaterials,
      },
    ];

    setFolderPaths(initialPath);
    setCopyList(initialPath);
  }, [toJsonTranslation, translate]);

  const listMaterial = React.useCallback(id => {
    const list = async () => {
      setIsLoaded(false);
      await getDownloadableMaterial({
        type: MATERIAL_TYPE.FILE,
        folderId: id,
      }).then(response => {
        setMaterials(response.data);
        setIsLoaded(true);
      });
    };

    list();
  }, []);

  const listFolders = React.useCallback(id => {
    const list = async () => {
      setIsLoaded(false);
      await getDownloadableMaterial({
        type: MATERIAL_TYPE.FOLDER,
        folderId: id,
      }).then(response => {
        setFolders(response.data);
        setIsLoaded(true);
      });
    };

    list();
  }, []);

  const deleteMaterial = React.useCallback(
    id => {
      const remove = async () => {
        await deleteDownloadableMaterial(id).then(() =>
          setMaterials(deleteItem({ itemId: id, list: materials }))
        );
      };

      remove();
    },
    [materials]
  );

  const deleteMaterialFolder = React.useCallback(
    id => {
      const remove = async () => {
        await deleteDownloadableMaterial(id).then(() =>
          setFolders(deleteItem({ itemId: id, list: folders }))
        );
      };

      remove();
    },
    [folders]
  );

  const createMaterialFolder = React.useCallback(materialFolderData => {
    const create = async () => {
      setIsLoading(true);
      await createDownloadableMaterial(materialFolderData).then(response => {
        setFolders(list => [...list, response.data]);
        setIsLoading(false);
      });
    };
    create();
  }, []);

  const createMaterial = React.useCallback(
    materialData => {
      const create = async () => {
        setIsLoading(true);
        await createDownloadableMaterial(materialData).then(response => {
          setMaterials(list => [...list, response.data]);
          history.goBack();
          setIsLoading(false);
        });
      };
      create();
    },
    [history]
  );

  const editMaterial = React.useCallback((materialData, id) => {
    const edit = async () => {
      setIsLoading(true);
      await updateDownloadableMaterial(materialData, id).then(response => {
        const responseMaterial = response.data;

        setMaterials(list => {
          const index = list.findIndex(obj => obj.id === responseMaterial.id);

          list[index] = responseMaterial;

          return [...list];
        });

        setIsLoading(false);
        openAdminDownloadMaterials();
      });
    };
    edit();
  }, []);

  const editFolder = React.useCallback((materialData, id) => {
    const edit = async () => {
      setIsLoading(true);
      await updateDownloadableMaterial(materialData, id).then(response => {
        const folder = response.data;

        setFolders(list => {
          const index = list.findIndex(obj => obj.id === folder.id);

          list[index] = folder;

          return [...list];
        });

        setIsLoading(false);
      });
    };
    edit();
  }, []);

  const showMaterial = React.useCallback(id => {
    const show = async () => {
      setIsLoading(true);
      await showDownloadableMaterial(id).then(response => {
        setMaterial(response.data);
        setMaterialTitle(response.data.title);
      });
      setIsLoading(false);
    };

    show();
  }, []);

  const showFolder = React.useCallback(id => {
    const show = async () => {
      setIsLoading(true);
      await showDownloadableMaterial(id).then(response => {
        setMaterialFolder(response.data);
        setFolderTitle(response.data.title);
      });
      setIsLoading(false);
    };

    show();
  }, []);

  const getTotalCount = React.useCallback(
    (startDate, endDate, materialId, granularity) => {
      const show = async () => {
        await getMaterialTotalCount(
          startDate,
          endDate,
          materialId,
          granularity
        ).then(response => {
          setMaterialTotalCount(response.data.total);
          setCardData(response.data.chart_data);
        });
        setIsLoaded(true);
      };

      show();
    },
    []
  );

  const getTotalCountAccess = React.useCallback(
    (startDate, endDate, search, page = 1, materialId) => {
      const show = async () => {
        setIsLoaded(false);
        await getMaterialUsersCountAccess(
          startDate,
          endDate,
          page,
          search,
          materialId
        ).then(response => {
          setTableData(response.data.data);
          setPagination({
            page: response.data.page,
            lastPage: response.data.lastPage,
          });
        });
        setIsLoaded(true);
      };

      show();
    },
    []
  );

  const renderFolderPaths = React.useCallback(
    (folderId, title) => {
      const folderIndex = copyList.findIndex(obj => obj.id === folderId);
      let newFolderList = [...copyList];

      if (folderIndex !== -1) {
        newFolderList.splice(
          folderIndex + 1,
          newFolderList.length - (folderIndex - 1)
        );
      } else {
        newFolderList = [
          ...copyList,
          {
            id: folderId,
            name: title,
            function: () => openFolderDetails(folderId),
          },
        ];
      }

      setCopyList(newFolderList);

      setFolderPaths(() => {
        if (newFolderList.length > 4) {
          const list = newFolderList.slice(-4);
          setSelectedFolderPath(list.length - 1);

          return list;
        }

        setSelectedFolderPath(newFolderList.length - 1);

        return newFolderList;
      });
    },
    [copyList]
  );

  return (
    <DownloadMaterialsContext.Provider
      value={{
        materials,
        material,
        setMaterial,
        folders,
        isLoaded,
        isLoading,
        materialTitle,
        materialTotalCount,
        cardData,
        tableData,
        pagination,
        folderTitle,
        materialFolder,
        folderPaths,
        setFolderPaths,
        selectedFolderPath,
        setFolderTitle,

        deleteMaterial,
        deleteMaterialFolder,
        createMaterialFolder,
        createMaterial,
        showMaterial,
        editMaterial,
        editFolder,
        listMaterial,
        listFolders,
        showFolder,
        getTotalCount,
        getTotalCountAccess,
        setMaterialFolder,
        renderFolderPaths,
      }}
    >
      {children}
    </DownloadMaterialsContext.Provider>
  );
};

DownloadMaterialsProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

function useDownloadMaterial() {
  const context = useContext(DownloadMaterialsContext);

  if (!context)
    throw new Error(
      "useDownloadMaterial must be used within DownloadMaterialsContext"
    );

  return context;
}

export { DownloadMaterialsProvider, useDownloadMaterial };
