import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Groups } from "./styles";

function EmailAudience({ selectedGroups, setSelectedGroups, ...rest }) {
  return (
    <Groups
      {...rest}
      selectedGroups={selectedGroups}
      setSelectedGroups={setSelectedGroups}
    />
  );
}

EmailAudience.propTypes = {
  selectedGroups: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedGroups: PropTypes.func.isRequired,
};

export default EmailAudience;
