import React from "react";
import { useParams } from "react-router-dom";
import PropTypes from "prop-types";
import * as Yup from "yup";

// CUSTOM IMPORTS
import { AdminFormModal as FormModal } from "~/modules/signed/modules/cms/components/AdminFormModal";
import { ROOM_VIEW_TYPE, MATERIAL_TYPE } from "~/shared/utils/types";
import { useTranslation } from "~/shared/hooks/translation";
import {
  TranslateInput,
  Fieldset,
  FieldsetTitle,
  Dropdown,
  Groups,
} from "./styles";

function CreateFolderModal({
  folder,
  onClose,
  onCreate,
  onEdit,
  editMode,
  ...rest
}) {
  const { folderId } = useParams();
  const [initialFormData, setInitialFormData] = React.useState(null);
  const [materialEditId, setMaterialEditId] = React.useState(null);
  const [selectedGroups, setSelectedGroups] = React.useState([]);
  const [viewType, setViewType] = React.useState("public");

  const { translate } = useTranslation();

  // FUNCTIONS
  const yupValidation = {
    title: Yup.string().required(translate("common.titleFolderIsRequired")),
  };

  function convertViewType() {
    switch (viewType) {
      case ROOM_VIEW_TYPE.PUBLIC:
        return translate("common.public");

      case ROOM_VIEW_TYPE.PRIVATE:
        return translate("common.private");

      case ROOM_VIEW_TYPE.HIDE:
        return translate("common.hide");

      default:
        return null;
    }
  }

  function handleOnChange(value) {
    setViewType(value);
  }

  const viewTypes = [
    { id: 1, name: translate("common.public"), value: ROOM_VIEW_TYPE.PUBLIC },
    { id: 2, name: translate("common.private"), value: ROOM_VIEW_TYPE.PRIVATE },
    { id: 3, name: translate("common.hide"), value: ROOM_VIEW_TYPE.HIDE },
  ];

  React.useEffect(() => {
    if (folder) {
      const { id, view_type, groups: groupIds } = folder;

      const groups = groupIds.map(g => g.id);

      setMaterialEditId(id);
      setSelectedGroups(groups);
      setViewType(view_type);

      if (editMode) {
        setInitialFormData(folder);
      } else {
        setInitialFormData(null);
      }
    }
  }, [editMode, folder]);

  const handleSubmit = async data => {
    const formattedFolder = {
      ...data,
      folder_id: folderId,
      type: MATERIAL_TYPE.FOLDER,
      view_type: viewType,
      group_ids: selectedGroups,
    };
    if (editMode) {
      onEdit(formattedFolder, materialEditId);
    } else {
      onCreate(formattedFolder);
    }
    onClose();
  };

  return (
    <FormModal
      {...rest}
      title={
        editMode
          ? translate("common.editFolder")
          : translate("common.newFolder")
      }
      onClose={onClose}
      yupValidation={yupValidation}
      onSubmit={handleSubmit}
      initialData={initialFormData}
      style={{ maxWidth: 500 }}
    >
      <TranslateInput
        name="title"
        title={translate("common.nameFolder")}
        placeholder={translate("common.typeNameFolder")}
      />

      <Fieldset>
        <FieldsetTitle>
          {translate(
            "entities.downloadMaterials.segmentationTab.fieldsetTitle"
          )}
        </FieldsetTitle>
        <Dropdown
          name="view_type"
          options={viewTypes}
          noRegistration
          initialValue={convertViewType()}
          onChange={handleOnChange}
        />
      </Fieldset>
      <Groups
        selectedGroups={selectedGroups}
        setSelectedGroups={setSelectedGroups}
      />
    </FormModal>
  );
}

CreateFolderModal.propTypes = {
  folder: PropTypes.object,
  onClose: PropTypes.func,
  onCreate: PropTypes.func,
  onEdit: PropTypes.func,
  editMode: PropTypes.bool,
};

CreateFolderModal.defaultProps = {
  folder: null,
  onClose: () => {},
  onCreate: () => {},
  onEdit: () => {},
  editMode: false,
};

export default CreateFolderModal;
