import React from "react";
import * as Yup from "yup";
import PropTypes from "prop-types";

// APIs
import {
  createPoster,
  updatePoster,
} from "~/modules/signed/modules/cms/modules/admin/apis/posters";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { openAdminPosters } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { Input, TranslateInput, Footer } from "./styles";

function PosterForm({ poster }) {
  const editMode = isDataValid(poster);
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  // STATES
  const [posterData, setPosterData] = React.useState(poster || {});
  const [showFooterModal, setShowFooterModal] = React.useState(false);
  const [isCreating, setIsCreating] = React.useState(false);
  const [isUpdating, setIsUpdating] = React.useState(false);

  // FUNCTIONS
  const onConfirm = async () => {
    if (!editMode) {
      setIsCreating(true);

      await createPoster({ posterData })
        .then(() => {
          setIsCreating(false);
          openAdminPosters();
        })
        .catch(() => setIsCreating(false));
    } else {
      setIsUpdating(true);

      await updatePoster({ posterData, posterId: poster.id })
        .then(() => {
          setIsUpdating(false);
          openAdminPosters();
        })
        .catch(() => setIsUpdating(false));
    }
  };

  return (
    <Form
      onSubmit={data => {
        setShowFooterModal(true);
        setPosterData(data);
      }}
      yupValidation={{
        title: Yup.string().required(
          translate("entities.posters.titleRequired")
        ),
        author: Yup.string().required(
          translate("entities.posters.authorRequired")
        ),
        co_author: Yup.string(),
        download_url: Yup.string(),
      }}
      initialData={poster}
    >
      <TranslateInput
        isRequired
        type="text"
        title={translate("entities.posters.inputTitle.title")}
        name="title"
        placeholder={translate("entities.posters.inputTitle.placeholder")}
      />

      <Input
        isRequired
        type="text"
        title={translate("entities.posters.inputAuthor.title")}
        name="author"
        placeholder={translate("entities.posters.inputAuthor.placeholder")}
      />

      <Input
        type="text"
        title={translate("entities.posters.inputCoAuthor.title")}
        name="co_author"
        placeholder={translate("entities.posters.inputCoAuthor.placeholder")}
      />

      <Input
        type="text"
        title={translate("entities.posters.inputLinkDowndload.title")}
        name="download_url"
        placeholder={translate(
          "entities.posters.inputLinkDowndload.placeholder"
        )}
      />

      <TranslateInput
        isTextArea
        type="text"
        title={translate("entities.posters.inputResume.title")}
        name="description"
        placeholder={translate("entities.posters.inputResume.placeholder")}
      />

      <Footer
        type="submit"
        showModal={showFooterModal}
        modalType="create"
        entity="Pôster"
        entityName={isDataValid(posterData) && dTranslate(posterData.title)}
        onCancel={openAdminPosters}
        onConfirm={onConfirm}
        isLoading={isCreating || isUpdating}
      />
    </Form>
  );
}

PosterForm.propTypes = {
  poster: PropTypes.object,
};

PosterForm.defaultProps = {
  poster: null,
};

export default PosterForm;
