import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { NotificationCardDefaultProps } from "./propTypes";
import { DefaultNotificationCard } from "./Default";
import { PopUpNotificationCard } from "./PopUp";
import { footerButtonsDefaultProps } from "..";

const NotificationCardPropTypes = {
  /** Render an Title in the card */
  title: PropTypes.string.isRequired,

  /** Add a notification text in the card  */
  description: PropTypes.string,

  /** Name of the icon  */
  icon: PropTypes.string,

  /** Unread link text  */
  unreadLink: PropTypes.string,

  /** Mark the notification as read or unread  */
  isReaded: PropTypes.bool,

  /** Time sent  */
  time: PropTypes.string,

  /** Callback that is triggered when user clicks into <b>UnreadLink</b> */
  onLinkClick: PropTypes.func,

  /** Callback triggered when user clicks into <b>NotificationCard</b>  */
  onClick: PropTypes.func,

  /** Callback triggered when user clicks into <b>delete</b> option.
   *
   * <b>obs: default variant only</b>
   */
  onDelete: PropTypes.func,

  /** Determines the status of the component.
   *
   * <b>obs: default variant only</b>
   */
  isLoaded: PropTypes.bool,

  /** This props is only works for variant <b>popup</b>.
   * Define the mili seconds to close the Notification.
   *
   *  <b>obs: popup variant only</b>
   *  */
  autoClose: PropTypes.number,

  /** Callback that is triggered when <b>popup variant</b> autoClose.
   *
   * <b>obs: popup variant only</b>
   */
  onAutoClose: PropTypes.func,

  /** Define the layout variant of the card */
  variant: PropTypes.oneOf(["default", "popup"]),

  /** Define the <b>unreadLink</b> will be rendered */
  hasLink: PropTypes.bool,

  /** Control all props for <b>FooterButtons</b> component. See all props into <b>molecules/FooterButtons</b> */
  footerButtons: PropTypes.shape(footerButtonsDefaultProps),
};

const NotificationCard = ({ variant, ...rest }) => {
  const renderNotification = () => {
    switch (variant) {
      case "popup":
        return <PopUpNotificationCard {...rest} />;

      default:
        return <DefaultNotificationCard {...rest} />;
    }
  };

  return renderNotification();
};

NotificationCard.propTypes = NotificationCardPropTypes;

NotificationCard.defaultProps = NotificationCardDefaultProps;

export {
  NotificationCard,
  NotificationCardPropTypes,
  NotificationCardDefaultProps,
};
