import React from "react";
import { useParams } from "react-router-dom";

// APIs
import { showPoster } from "~/modules/signed/modules/cms/modules/admin/apis/posters";

// CUSTOM IMPORTS
import { openAdminPosters } from "~/shared/routes/navigation";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import PosterForm from "~/modules/signed/modules/cms/modules/admin/pages/Posters/pages/components/PosterForm";
import { Spinner } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

function EditPoster() {
  const { posterId } = useParams();
  const [poster, setPoster] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: `${translate("entities.posters.plural")}`,
        function: openAdminPosters,
      },
      {
        id: 2,
        name: `${translate("entities.posters.edit")}`,
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  React.useEffect(() => {
    const show = async () => {
      await showPoster({ posterId })
        .then(response => {
          setPoster(response.data);
          setIsLoaded(true);
        })
        .catch(() => setIsLoaded(true));
    };

    show();
  }, [posterId]);

  return isLoaded ? <PosterForm poster={poster} /> : <Spinner centered />;
}

export default EditPoster;
