import api from "~/shared/services/api";

export const showPartnerMeeting = ({ partnerId }) =>
  api.get(`/meeting/partner/${partnerId}`);

/** This API will return all partner meeting avaiable dates  */
export const getPartnerAvailableDates = ({ partnerId }) =>
  api.get("meeting_config/available_dates", {
    params: { partner_id: partnerId },
  });
