import React from "react";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

const LabeledAvatarLoader = props => {
  const { colors } = React.useContext(ThemeContext);
  return (
    <ContentLoader
      speed={2}
      width={300}
      height={80}
      viewBox="0 0 300 80"
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
      {...props}
    >
      <rect x="96" y="46" rx="3" ry="3" width="145" height="6" />
      <rect x="96" y="25" rx="3" ry="3" width="120" height="6" />
      <rect x="321" y="196" rx="3" ry="3" width="410" height="6" />
      <rect x="543" y="206" rx="3" ry="3" width="380" height="6" />
      <rect x="462" y="193" rx="3" ry="3" width="178" height="6" />
      <rect x="0" y="0" rx="8" ry="8" width="80" height="80" />
      <rect x="95" y="23" rx="0" ry="0" width="56" height="0" />
    </ContentLoader>
  );
};

export default LabeledAvatarLoader;
