import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const limiterPropTypes = {
  /** The current number  */
  value: PropTypes.number,

  /** The limit for value */
  limit: PropTypes.number,
};

const limiterDefaultProps = {
  value: 0,
  limit: 100,
};

const Limiter = ({ value, limit, ...rest }) => {
  return (
    <Container value={value} limit={limit} {...rest}>
      {value}/{limit}
    </Container>
  );
};

Limiter.propTypes = limiterPropTypes;
Limiter.defaultProps = limiterDefaultProps;

export { Limiter, limiterPropTypes, limiterDefaultProps };
