import React, { createContext } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";

//* ACTIONS
import { useParams } from "react-router-dom";
import {
  receiveBlockedUsers,
  receiveMessages,
  receivePinnedMessage,
  receiveSingleMessage,
  receiveSingleMessageLikes,
} from "../../../store/broadcastRoomChat/actions";

//* CUSTOM IMPORTS
import { useSocket } from "~/modules/signed/hooks/webSocket";

const BrRoomChatSocketContext = createContext(null);

const BrRoomChatSocketProvider = ({ children }) => {
  const { subscribe, closeChannel } = useSocket();
  const dispatch = useDispatch();
  const { broadcastRoomId } = useParams();

  //* FUNCTIONS
  React.useEffect(() => {
    const channel = subscribe(`messageroom:${broadcastRoomId}`);

    if (channel) {
      channel.on("ready", () => {
        channel.emit("getMessages", { page: 1 });
      });

      //* * Receive a single message
      channel.on("single_message", message => {
        dispatch(receiveSingleMessage(message));
      });

      //* * Receive the pinned message */
      channel.on("receive_pinned_message", pinnedMessage => {
        dispatch(receivePinnedMessage({ pinnedMessage }));
      });

      //* * Receive all historic messages
      channel.on("receive_messages", response => {
        const messages = response.data;

        dispatch(receiveMessages(messages));
      });

      channel.on("single_message_likes", message => {
        dispatch(receiveSingleMessageLikes({ message }));
      });

      //* * Receive a list without users blocked messages
      channel.on("receive_filtered_messages", response => {
        const filteredMessages = response.data;

        dispatch(receiveMessages(filteredMessages));
      });

      //* * This event return an list with blocked users IDs
      channel.on("blocked_users", response => {
        dispatch(receiveBlockedUsers(response));
      });
    }

    return () => {
      closeChannel(channel);
    };
  }, [broadcastRoomId, closeChannel, dispatch, subscribe]);

  return (
    <BrRoomChatSocketContext.Provider value={{}}>
      {children}
    </BrRoomChatSocketContext.Provider>
  );
};

BrRoomChatSocketProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useBrRoomChatSocket() {
  const context = React.useContext(BrRoomChatSocketContext);

  if (!context)
    throw new Error(
      "useBrRoomChatSocket must be used within BrRoomChatSocketProvider"
    );

  return context;
}

export { BrRoomChatSocketProvider, useBrRoomChatSocket };
