import React from "react";
import PropTypes from "prop-types";

import UserAttributes from "./UserAttributeList";

const generalTabPropTypes = {
  user: PropTypes.object.isRequired,
  callIf: PropTypes.bool.isRequired,
};

const GeneralTab = ({ user, callIf }) => {
  return <UserAttributes user={user} callIf={callIf} />;
};

GeneralTab.propTypes = generalTabPropTypes;

export { GeneralTab, generalTabPropTypes };
