import React from "react";
import PropTypes from "prop-types";
import { useSigned } from "~/modules/signed/hooks";

const StickyNotesContext = React.createContext(null);

const INITIAL_DATA = {
  data: [],
  isLoaded: false,
  page: 1,
  lastPage: 1,
};

const StickyNotesProvider = ({ children }) => {
  const { user } = useSigned();

  //* STATES
  const [stickyNotes, setStickyNotes] = React.useState(INITIAL_DATA);
  const [stickyNoteComments, setStickyNoteComments] = React.useState(
    INITIAL_DATA
  );

  const author = { id: user.id, avatar: user.avatar, name: user.name };

  //* FUNCTIONS
  const handleStickyNoteComments = React.useCallback(
    (stickyNoteId, increase) => {
      setStickyNotes(state => {
        const index = state.data.findIndex(s => s.id === stickyNoteId);

        if (index !== -1) {
          const lastMeta = state.data[index].__meta__;
          const increment = increase ? 1 : -1;

          if (!lastMeta) {
            state.data[index].__meta__ = {
              total_comments: 1,
            };
          } else {
            state.data[index].__meta__ = {
              ...lastMeta,
              total_comments: lastMeta.total_comments
                ? Number(lastMeta.total_comments) + increment
                : 1,
            };
          }
        }

        return { ...state };
      });
    },
    []
  );

  /** Increment or decrement stickyNote like count, based if it is already liked. */
  const handleStickyNoteLike = React.useCallback(stickyNoteId => {
    setStickyNotes(state => {
      const index = state.data.findIndex(s => s.id === stickyNoteId);

      if (index !== -1) {
        const isLiked = state.data[index].is_liked;
        const lastMeta = state.data[index].__meta__;
        const increment = isLiked ? -1 : 1;
        let newMeta;

        if (!lastMeta) {
          newMeta = {
            total_likes: 1,
          };
        } else {
          newMeta = {
            ...lastMeta,
            total_likes: lastMeta.total_likes
              ? Number(lastMeta.total_likes) + increment
              : 1,
          };
        }

        state.data[index].__meta__ = newMeta;
        state.data[index].is_liked = !isLiked;
      }

      return { ...state };
    });
  }, []);

  /** Increment or decrement stickyNoteComment like count, based if it is already liked. */
  const handleStickyNoteCommentLike = React.useCallback(commentId => {
    setStickyNoteComments(state => {
      const index = state.data.findIndex(s => s.id === commentId);

      if (index !== -1) {
        const isLiked = state.data[index].is_liked;
        const lastMeta = state.data[index].__meta__;
        const increment = isLiked ? -1 : 1;
        let newMeta;

        if (!lastMeta) {
          newMeta = {
            total_reactions: 1,
          };
        } else {
          newMeta = {
            ...lastMeta,
            total_reactions: lastMeta.total_reactions
              ? Number(lastMeta.total_reactions) + increment
              : 1,
          };
        }

        state.data[index].__meta__ = newMeta;
        state.data[index].is_liked = !isLiked;
      }

      return { ...state };
    });
  }, []);

  const addStickyNote = React.useCallback(
    response => {
      setStickyNotes(state => {
        return {
          ...state,
          data: [
            {
              ...response,
              author,
            },
            ...state.data,
          ],
        };
      });
    },
    [author]
  );

  const paginateStickNotes = React.useCallback(response => {
    setStickyNotes(state => ({
      ...state,
      ...response,
      data: [...state.data, ...response.data],
    }));
  }, []);

  const receiveStickNotes = React.useCallback(response => {
    setStickyNotes({ ...response, isLoaded: true });
  }, []);

  const deleteStickNote = React.useCallback(id => {
    setStickyNotes(state => {
      const filteredArray = state.data.filter(s => s.id !== id);

      return { ...state, data: filteredArray };
    });
  }, []);

  const clearStickyNotes = React.useCallback(() => {
    setStickyNotes(INITIAL_DATA);
  }, []);

  const clearStickyNoteComments = React.useCallback(() => {
    setStickyNoteComments(INITIAL_DATA);
  }, []);

  const receiveStickyNoteComments = React.useCallback(response => {
    setStickyNoteComments({ ...response, isLoaded: true });
  }, []);

  const paginateStickyNoteComments = React.useCallback(response => {
    setStickyNoteComments(state => ({
      ...state,
      ...response,
      data: [...state.data, ...response.data],
    }));
  }, []);

  const addStickyNoteComment = React.useCallback(
    response => {
      const { sticky_note_id: stickyNoteId } = response;

      handleStickyNoteComments(stickyNoteId, true);

      setStickyNoteComments(state => ({
        ...state,
        data: [{ ...response, author }, ...state.data],
      }));
    },
    [author, handleStickyNoteComments]
  );

  const deleteStickNoteComment = React.useCallback(
    (commentId, stickyNoteId) => {
      handleStickyNoteComments(stickyNoteId, false);

      setStickyNoteComments(state => {
        const filteredArray = state.data.filter(s => s.id !== commentId);

        return { ...state, data: filteredArray };
      });
    },
    [handleStickyNoteComments]
  );

  return (
    <StickyNotesContext.Provider
      value={{
        //* States
        stickyNotes,
        stickyNoteComments,

        //* Functions
        addStickyNote,
        deleteStickNote,
        clearStickyNotes,
        receiveStickNotes,
        paginateStickNotes,
        addStickyNoteComment,
        handleStickyNoteLike,
        deleteStickNoteComment,
        clearStickyNoteComments,
        receiveStickyNoteComments,
        paginateStickyNoteComments,
        handleStickyNoteCommentLike,
      }}
    >
      {children}
    </StickyNotesContext.Provider>
  );
};

StickyNotesProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useStickyNotes = () => {
  const context = React.useContext(StickyNotesContext);

  if (!context)
    throw new Error("useStickyNotes must be used within StickyNotesProvider");

  return context;
};

export { StickyNotesProvider, useStickyNotes };
