import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS
import { SearchBar } from "../../styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { NetworkRoomCell } from "~/shared/components/organisms/cells/NetworkRoomCell";
import NetworkCellLoader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";
import { openMarketRoom } from "~/shared/routes/navigation";
import { isListValid } from "~/shared/utils/validations";
import { BookmarkButton } from "~/modules/signed/modules/client/components/buttons/BookmarkButton";

function NetworkRooms({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkNetworkRooms,
    isSearching,
    networkRoomsCurrentPage,
    searchString,
    setSearchString,
    paginateBookmarks,
    listBookmarks,
    setBookmarkNetworkRooms,
  } = useBookmarkProfile();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (!bookmarkNetworkRooms.isLoaded && isFetcheable)
      listBookmarks(
        bookmarkNetworkRooms.page,
        "network_rooms",
        setBookmarkNetworkRooms
      );
  }, [
    bookmarkNetworkRooms.isLoaded,
    bookmarkNetworkRooms.page,
    isFetcheable,
    listBookmarks,
    setBookmarkNetworkRooms,
  ]);

  // FUNCTIONS
  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="networkRooms" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="networkRooms" />}
      />
    );
  };

  const renderCell = networkRoom => {
    const { room, video_call_url: videoCallUrl } = networkRoom;

    return (
      <NetworkRoomCell
        title={room.title}
        description={room.description}
        image={room.logo}
        tags={room.tags}
        id={room.id}
        customButton={
          <BookmarkButton
            isSelected={room.bookmark}
            apiType="rooms"
            apiId={room.id}
            variant="icon"
          />
        }
        bookmark={room.bookmark}
        bottomTags={
          isListValid(room.partners) &&
          room.partners.map(partner => ({
            name: partner.title,
            onClick: () => openMarketRoom(partner.id, partner.title),
            hoverable: true,
          }))
        }
        onClick={() => window.open(videoCallUrl)}
        isBlocked={!room.allow_access}
      />
    );
  };
  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);

          listBookmarks(
            bookmarkNetworkRooms.page,
            "network_rooms",
            setBookmarkNetworkRooms,
            value
          );
        }}
      />

      <CellList
        isLoaded={isUserLoaded && !isSearching}
        isPaging={bookmarkNetworkRooms.isPaging}
        renderCellLoader={() => <NetworkCellLoader />}
        loaderListSize={3}
        gap={20}
        data={bookmarkNetworkRooms.data}
        renderCell={room => renderCell(room)}
        emptyComponent={handleEmptyMessage()}
      />

      <Pagination
        style={{ marginTop: 16 }}
        currentPage={networkRoomsCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.NETWORK, page);
        }}
        lastPage={bookmarkNetworkRooms.lastPage}
      />
    </div>
  );
}

NetworkRooms.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { NetworkRooms };
