import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Form, SwipeTabs } from "~/shared/components/molecules";

// TAB FRAGMENTS
import PerformanceTab from "./components/PerformanceTab";
import InformationTab from "./components/InformationTab";
import AudienceTab from "./components/AudienceTab";

import { useTranslation } from "~/shared/hooks/translation";

function DetailsForm({ email, log }) {
  const { translate } = useTranslation();

  // TABS
  const tabs = [
    { id: 1, name: translate("common.performance") },
    { id: 2, name: translate("common.informations") },
    { id: 3, name: translate("common.public") },
  ];

  return (
    <>
      <Form id="performance-form">
        <SwipeTabs
          size="small"
          alignment="left"
          tabs={tabs}
          childrenStyle={{ padding: 4 }}
        >
          <PerformanceTab log={log} />

          <InformationTab email={email} />

          <AudienceTab email={email} />
        </SwipeTabs>
      </Form>
    </>
  );
}

DetailsForm.propTypes = {
  email: PropTypes.object.isRequired,
  log: PropTypes.object.isRequired,
};

DetailsForm.defaultProps = {};

export default DetailsForm;
