import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Card as CardC, Typography, HoverWrapper } from "../../../atoms";
import { CRUDOptionsMenu as CRUDOptionsMenuC } from "../../CRUDOptionsMenu";

export const Touchable = styled(HoverWrapper)`
  position: relative;
  width: 300px;

  &:hover,
  &:active {
    .options {
      opacity: 1;
      transition: opacity 0.5s;
    }
  }

  ${props =>
    props.isBlocked &&
    css`
      pointer-events: none;
      cursor: not-allowed;
    `}
`;

export const Card = styled(CardC)`
  width: 300px;
  height: 56px;
  background: transparent;
  cursor: pointer;
  border: 2px solid ${props => props.theme.colors.shadow};
  box-shadow: none;
  border-radius: 6px;
  padding: 0;

  display: flex;
  justify-content: space-around;
`;

export const Options = styled(CRUDOptionsMenuC)`
  opacity: 0;
  right: 0px;
  color: ${props => props.theme.colors.shadow};
`;

export const Title = styled(Typography)`
  font-size: 16px;
  font-weight: bold;
  padding-left: 16px;
`;

export const Content = styled.div`
  width: 100%;
  padding-left: 20px;

  display: flex;
  align-items: center;

  svg {
    height: 24px;
    width: 24px;
    opacity: 0.5;
    margin-bottom: 0;

    ${props =>
      props.isBlocked &&
      css`
        opacity: 0;
      `}
  }
`;

export const PrivateOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 56px;
  width: 100%;
  border-radius: 8px;
  cursor: normal;
  background: ${props => props.theme.colors.primary};
  opacity: 1;
`;

export const PrivateOverlayIcon = styled.div`
  position: absolute;
  z-index: 1;
  height: 35px;
  width: 35px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: normal;
  background: ${props => props.theme.colors.surface};

  svg {
    color: ${props => props.theme.colors.primary};
  }
`;
