import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";

//* CUSTOM IMPORTS
import { TranslationInput } from "~/shared/components/organisms";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { Container } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

const adminTranslateInputPropTypes = {
  name: PropTypes.string.isRequired,
  className: PropTypes.string,
  style: PropTypes.object,
};

const adminTranslateInputDefaultProps = {
  className: null,
  style: null,
};

/** This component is responsable to convert all CMS translations and to return it to an Form */
const AdminTranslateInput = ({ name, className, style, ...rest }) => {
  const {
    currentLanguage: globalLanguage,
    languages,
    getLangNameByRefCode,
    getTranslateByRefCode,
    createJsonTranslation,
    updateTranslateByRefCode,
  } = useDynamicTranslation();
  const { translate } = useTranslation();
  const { defaultValue } = useField(name);
  const { fieldName, registerField } = useField(`${name}-translation`);

  //* REF
  const inputRef = React.useRef(null);

  //* STATES
  const [inputLanguage, setInputLanguage] = React.useState(globalLanguage);
  const [values, setValues] = React.useState([]);
  const [value, setValue] = React.useState();
  const [warning, setWarning] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, inputRef, registerField]);

  React.useEffect(() => setValues(createJsonTranslation(defaultValue)), [
    createJsonTranslation,
    defaultValue,
  ]);

  /** Change inputLanguage every time the globalLang changes */
  React.useMemo(() => globalLanguage && setInputLanguage(globalLanguage), [
    globalLanguage,
  ]);

  /** Change inputValue every time inputLang changes */
  /** Change inputValue every time inputLang changes */
  React.useMemo(() => {
    if (inputLanguage && values)
      setValue(getTranslateByRefCode(values, inputLanguage.refCode));
  }, [getTranslateByRefCode, inputLanguage, values]);

  /** Add the value of the current language into the JSON values */
  const handleOnChange = e => {
    const inputValue = e.target.value;

    setValue(inputValue);

    const jsonValue = updateTranslateByRefCode(
      inputValue,
      values,
      inputLanguage.refCode
    );

    /** Verify if has some language that do not have data */
    if (jsonValue) {
      const langsWithoutData = Object.keys(jsonValue).filter(
        (_, index) => Object.values(jsonValue)[index] === ""
      );

      if (langsWithoutData.length) {
        setWarning(
          `${translate(
            langsWithoutData.length > 1
              ? "components.translateInput.warning.plural.part1"
              : "components.translateInput.warning.singular.part1"
          )} ${langsWithoutData.map(lang =>
            getLangNameByRefCode(lang)
          )} ${translate(
            langsWithoutData.length > 1
              ? "components.translateInput.warning.plural.part2"
              : "components.translateInput.warning.singular.part2"
          )}`
        );
      } else {
        setWarning(null);
      }
    }

    setValues(() => jsonValue);
  };

  return (
    <Container className={className} style={style}>
      <TranslationInput
        {...rest}
        value={value}
        defaultValue={value}
        name={name}
        id={name}
        warning={warning}
        onChange={handleOnChange}
        countryCode={inputLanguage && inputLanguage.countryCode}
        languageMenu={{
          data: languages.map(language => ({
            id: language.ref_code,
            name: language.title,
            countryCode: language.flag_icon,
          })),
          onFlagClick: flag => setInputLanguage({ ...flag, refCode: flag.id }),
        }}
      />

      <input
        ref={inputRef}
        style={{ display: "none" }}
        value={JSON.stringify(values)}
      />
    </Container>
  );
};

AdminTranslateInput.propTypes = adminTranslateInputPropTypes;
AdminTranslateInput.defaultProps = adminTranslateInputDefaultProps;

export { AdminTranslateInput };
