import React from "react";
import PropTypes from "prop-types";
import Icon from "@mdi/react";
import { mdiCertificateOutline } from "@mdi/js";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import {
  Container,
  PreviewButton,
  TranslateTextEditor,
  FileInput,
} from "./styles";
import { Typography } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import CertificatePreviewModal from "./CertificatePreviewModal";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function AppearanceTab({
  certificateBody,
  onTextEditorChange,
  onBackgroundChange,
  onFooterChange,
  background,
  footer,
  showSchedule,
  broadcastRoomIds,
}) {
  const { getTranslateByRefCode, currentLanguage } = useDynamicTranslation();
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);
  const textEditorInitialHtml = `<br/><br/><h1 class="ql-align-center"><br></h1><h1 class="ql-align-center"><br></h1><h1 class="ql-align-center"><strong class="ql-size-huge" style="color: ${colors.accent};">CERTIFICADO</strong></h1><p class="ql-align-center"><strong class="ql-size-large" style="color: ${colors.accent};">N° do registro: {{NREGISTRO}}</strong></p><p class="ql-align-center"><br></p><h2 class="ql-align-center"><br></h2><p class="ql-align-center"><span class="ql-size-huge">O Sistema Regional de Inovação do Norte Pioneiro do Paraná, certifica que: </span><strong class="ql-size-huge">{{NOME}}</strong><span class="ql-size-huge">, CPF: </span><strong class="ql-size-huge">{{CPF}}</strong><span class="ql-size-huge">, fez parte da comissão organizadora da </span><strong class="ql-size-huge">{{NOME_EVENTO}}.</strong></p><p class="ql-align-center"><br></p><p class="ql-align-center"><br></p><p class="ql-align-center"><strong class="ql-size-huge">CARGA HORÁRIO DE </strong><strong class="ql-size-huge" style="color: ${colors.accent};">{{WORKLOAD}}</strong></p><br/><br/><br/><br/><br/><br/>`;

  //* STATES
  const [textEditorLang, setTextLangEditor] = React.useState(null);
  const [showPreview, setShowPreview] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => setTextLangEditor(currentLanguage), [currentLanguage]);

  const openPreviewModal = () => {
    setShowPreview(true);
  };

  return (
    <Container>
      <PreviewButton
        type="button"
        title={translate("entities.certificates.view")}
        icon={<Icon path={mdiCertificateOutline} />}
        onClick={openPreviewModal}
      >
        {translate("entities.certificates.view")}
      </PreviewButton>

      <Typography
        className="small bold halfOpaque uppercase"
        style={{ marginBottom: 8 }}
      >
        {translate("entities.certificates.content")}
      </Typography>

      <TranslateTextEditor
        name="body_message"
        onLanguageChange={lang => setTextLangEditor(lang)}
        onChange={value => onTextEditorChange(value)}
        variables={[
          { id: 1, name: "CPF", value: "CPF" },
          { id: 2, name: `${translate("common.name")}`, value: "NOME" },
          { id: 3, name: "Email", value: "EMAIL" },
          {
            id: 4,
            name: `${translate("common.eventName")}`,
            value: "NOME_EVENTO",
          },
          { id: 5, name: `${translate("common.workload")}`, value: "WORKLOAD" },
          {
            id: 6,
            name: `${translate("common.registerNumber")}`,
            value: "NREGISTRO",
          },
        ]}
      />

      <div id="bottom">
        <FileInput
          title={translate("entities.certificates.InputBackground.title")}
          name="bg"
          width={1366}
          heigth={965}
          onUploadFinish={url => onBackgroundChange(url)}
          initialValue={background}
        />

        <FileInput
          title={translate("entities.certificates.InputFooter.title")}
          name="footer"
          width={1366}
          heigth={308}
          onUploadFinish={url => onFooterChange(url)}
          initialValue={footer}
        />
      </div>

      {showPreview && (
        <CertificatePreviewModal
          show={showPreview}
          onClose={() => setShowPreview(false)}
          bodyMessage={
            getTranslateByRefCode(certificateBody, textEditorLang.refCode) ||
            textEditorInitialHtml
          }
          background={background}
          footer={footer}
          showSchedule={showSchedule}
          broadcastRoomIds={broadcastRoomIds}
        />
      )}
    </Container>
  );
}

AppearanceTab.propTypes = {
  certificateBody: PropTypes.string.isRequired,
  onTextEditorChange: PropTypes.func.isRequired,
  background: PropTypes.string.isRequired,
  footer: PropTypes.string.isRequired,
  onBackgroundChange: PropTypes.func.isRequired,
  onFooterChange: PropTypes.func.isRequired,
  showSchedule: PropTypes.bool.isRequired,
  broadcastRoomIds: PropTypes.arrayOf(PropTypes.number).isRequired,
};

export default AppearanceTab;
