import React, { useEffect, createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIs
import { getIsRegistrationEnabled } from "~/modules/auth/apis/register";

//* CUSTOM IMPORTS
import { Spinner } from "~/shared/components/atoms";
import { AuthTemplate } from "~/shared/components/templates";
import { WhatsAppButton } from "~/shared/components/molecules";
import { useApp } from "~/shared/hooks";

const AuthContext = createContext(null);

const AuthProvider = ({ children }) => {
  //* STATES
  const { whatsAppButton } = useApp();
  const [isRegistrationEnabled, setIsRegistrationEnabled] = React.useState(
    true
  );
  const [isLoaded, setIsLoaded] = React.useState(false);

  useEffect(() => {
    const fetchRegistrationEnebled = async () => {
      await getIsRegistrationEnabled().then(response => {
        setIsRegistrationEnabled(response.data.register);
        setIsLoaded(true);
      }, {});
    };

    fetchRegistrationEnebled();
  }, []);

  return (
    <AuthContext.Provider value={{ isRegistrationEnabled }}>
      <AuthTemplate>
        {!isLoaded ? <Spinner centered size="larger" /> : children}
      </AuthTemplate>

      {whatsAppButton && <WhatsAppButton link={whatsAppButton} />}
    </AuthContext.Provider>
  );
};

AuthProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useAuth() {
  const context = useContext(AuthContext);

  if (!context) throw new Error("useAuth must be used within AuthProvider");

  return context;
}

export { AuthProvider, useAuth };
