import { createBrowserHistory } from "history";

//* CUSTOM IMPORTS
import { isDataValid } from "../utils/validations";

const history = createBrowserHistory();

export const pushState = (path, state = null) => {
  let code = null;

  if (isDataValid(state)) if (isDataValid(state.code)) code = state.code;

  history.push(path, {
    ...state,

    code,
  });
};

export const popState = () => history.goBack();

export default history;
