import React from "react";
import { MdToday } from "react-icons/md";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { getFullHours } from "~/shared/utils/dates";
import { ScheduleCell } from "~/shared/components/organisms/cells";
import CellLoader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { Typography, Trans } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";

const propTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  /** If False display the list in a loading mode */
  isLoaded: PropTypes.bool,

  /** Callback that is triggered when user click into an ScheduleCell,
   *  return the data of that schedule */
  onCellClick: PropTypes.func,

  /** Add an style into the cell that correspond the id */
  selectedCellId: PropTypes.number,

  /** Add an element  below the component selected */
  cellFooterElement: PropTypes.element,

  /** Display an message when list is emptyu, use <b>Empty</b> component. */
  empty: PropTypes.shape({
    message: PropTypes.string,
    description: PropTypes.string,
    icon: PropTypes.element,
  }),

  /** */
  renderCustomButton: PropTypes.element,
};

const defaultProps = {
  isLoaded: false,
  empty: null,
  onCellClick: () => {},
  selectedCellId: null,
  cellFooterElement: null,
  renderCustomButton: null,
};

function ScheduleTabList({
  data,
  isLoaded,
  onCellClick,
  empty,
  selectedCellId,
  cellFooterElement,
  renderCustomButton,
  ...rest
}) {
  const renderCell = cell => (
    <CellList
      firstCell={
        <Typography className="small opaque bold" style={{ marginBottom: 10 }}>
          {cell.date}
        </Typography>
      }
      orientation="column"
      isLoaded={isLoaded}
      data={cell.data}
      renderCell={s => {
        const {
          title: scheduleTitle,
          speakers,
          start_time,
          end_time,
          id,
          bookmark,
        } = s;

        return (
          <div className="schedule-cell-container" style={{ width: "100%" }}>
            <ScheduleCell
              id={id}
              bookmark={bookmark}
              className="schedule-cell"
              disableMenu
              customButton={renderCustomButton(s)}
              isLive={s.broadcastRoom.on_live}
              title={scheduleTitle}
              speakers={speakers}
              startTime={getFullHours(start_time)}
              endTime={getFullHours(end_time)}
              style={{ marginBottom: 0 }}
              onClick={() => onCellClick(s)}
              variant={selectedCellId === s.id && "accent"}
            />

            {selectedCellId === s.id && cellFooterElement}
          </div>
        );
      }}
      cellStyle={{ marginBottom: 10, width: "100%" }}
    />
  );

  return (
    <CellList
      {...rest}
      orientation="column"
      isLoaded={isLoaded}
      data={data}
      renderCellLoader={() => (
        <CellLoader
          width={450}
          height={91}
          style={{ marginBottom: 10, width: "100%", height: 91 }}
        />
      )}
      cellStyle={{ width: "100%" }}
      loaderListSize={6}
      renderCell={renderCell}
      emptyComponent={
        <Empty
          message={
            empty ? (
              empty.message
            ) : (
              <Trans translationKey="components.scheduleTabList.empty.message">
                Sem programações
              </Trans>
            )
          }
          description={
            empty ? (
              empty.description
            ) : (
              <Trans translationKey="components.scheduleTabList.empty.description">
                Essa categoria aínda não possuí programações cadastradas
              </Trans>
            )
          }
          icon={empty ? empty.icon : <MdToday />}
        />
      }
    />
  );
}

ScheduleTabList.propTypes = propTypes;

ScheduleTabList.defaultProps = defaultProps;

export default ScheduleTabList;
