import React from "react";
import PropTypes from "prop-types";
import { shade, lighten } from "polished";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Spinner } from "../Spinner";
import { Tip } from "../index";
import { Container, MaterialButton, Icon, TextContainer } from "./styles";

const buttonPropTypes = {
  /** ClassName to control CSS from another component */
  className: PropTypes.string,

  /** If TRUE display an spinner */
  loading: PropTypes.bool,

  /** Add string inside button */
  children: PropTypes.string,

  /** Add string inside button */
  placeholder: PropTypes.string,

  /** SVG icon to be render */
  icon: PropTypes.node,

  /** Define the font color */
  fontColor: PropTypes.string,

  /** if TRUE shade the button background color */
  isSelected: PropTypes.bool,

  variant: PropTypes.oneOf([
    "text",
    "outlined",
    "contained",
    "primary",
    "error",
    "surface",
  ]),

  size: PropTypes.oneOf(["small", "regular", "large"]),

  /** Define the button background */
  backgroundColor: PropTypes.string,

  /** Define the button background */
  tipPosition: PropTypes.oneOf(["top", "botton", "right", "left"]),

  /** Define the min weight of the Tip */
  tipSize: PropTypes.number,

  /** If tip has some string, render an Tip when user hover the button */
  tip: PropTypes.element,

  style: PropTypes.object,

  /** By default <b>Tip</b> component is only enable if the <b>Button</b>
   * is not selected, this props will force <b>Tip</b> to appear even if
   * is Selected
   */
  forceTip: PropTypes.bool,
};

const buttonDefaultProps = {
  className: null,
  tipPosition: "botton",
  tipSize: 0,
  loading: false,
  icon: null,
  fontColor: null,
  children: null,
  placeholder: null,
  variant: "contained",
  size: "regular",
  style: null,
  isSelected: false,
  backgroundColor: null,
  tip: null,
  forceTip: false,
};

/** The default Button of the application */
function Button({
  className,
  loading,
  placeholder,
  variant,
  children,
  icon,
  fontColor,
  size,
  style,
  isSelected,
  backgroundColor,
  tip,
  tipPosition,
  tipSize,
  forceTip,
  ...rest
}) {
  const hasTip = () => {
    if (forceTip) return true;

    if (!isSelected && tip) return true;

    return false;
  };

  const { colors } = React.useContext(ThemeContext);

  const renderSpinnerColor = () => {
    switch (variant) {
      case "contained":
        return colors.surface;

      case "outlined":
        return colors.accent;

      case "text":
        return colors.opaque;

      case "primary":
        return colors.surface;

      default:
        return colors.surface;
    }
  };

  const renderVariant = () => {
    switch (variant) {
      case "contained":
        return "contained";

      case "outlined":
        return "outlined";

      case "text":
        return "text";

      case "primary":
        return "text";

      default:
        return "contained";
    }
  };

  const renderRippleColor = () => {
    if (variant === "primary") return shade(0.5, colors.primary);

    if (backgroundColor) return shade(0.3, backgroundColor);

    if (variant === "error") return shade(0.7, colors.redWrong);

    return null;
  };

  const renderHoverColor = () => {
    if (backgroundColor) return shade(0.15, backgroundColor);

    if (variant === "primary") return shade(0.15, colors.primary);

    if (variant === "error") return shade(0.15, colors.redWrong);

    if (variant === "surface") return shade(0.15, colors.surface);

    if (isSelected && variant === "outlined") return shade(0.15, colors.accent);

    if (isSelected && variant === "text") return lighten(0.3, colors.accent);

    return null;
  };

  const renderFontColor = () => {
    if (fontColor) return fontColor;

    if (isSelected && variant === "outlined") return colors.surface;

    if (isSelected && variant === "text") return colors.accent;

    switch (variant) {
      case "contained":
        return colors.surface;

      case "outlined":
        return colors.accent;

      case "text":
        return colors.opaque;

      case "primary":
        return colors.surface;

      case "surface":
        return colors.accent;

      default:
        return colors.surface;
    }
  };

  const renderBackgroundColor = () => {
    if (backgroundColor) return backgroundColor;

    if (
      isSelected &&
      (variant === "contained" || variant === "containedPrimary")
    )
      return shade(0.15, colors.accent);

    if (isSelected && variant === "outlined") return colors.accent;

    if (isSelected && variant === "text") return lighten(0.35, colors.accent);

    if (isSelected && variant === "primary") return shade(0.15, colors.primary);

    if (variant === "primary") return colors.primary;

    if (variant === "error") return colors.redWrong;

    if (variant === "surface") return colors.surface;

    return null;
  };

  return (
    <Container
      className={className}
      size={size}
      style={style}
      hoverColor={renderHoverColor()}
      rippleColor={renderRippleColor()}
      backgroundColor={renderBackgroundColor()}
      isSelected={isSelected}
    >
      <MaterialButton
        {...rest}
        variant={renderVariant()}
        color="secondary"
        fontColor={renderFontColor()}
      >
        {loading ? (
          <Spinner size="small" color={renderSpinnerColor()} />
        ) : (
          <>
            {icon && (
              <Icon
                color={renderFontColor()}
                hasChildren={placeholder || children}
              >
                {icon}
              </Icon>
            )}

            <TextContainer className="button-text-container">
              {placeholder || children}
            </TextContainer>
          </>
        )}
      </MaterialButton>

      {hasTip() && (
        <Tip
          className="button-tip"
          content={tip}
          direction={tipPosition}
          size={tipSize}
        />
      )}
    </Container>
  );
}

Button.propTypes = buttonPropTypes;
Button.defaultProps = buttonDefaultProps;

export { Button, buttonPropTypes, buttonDefaultProps };
