import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Paragraph } from "./styles";

const typographyPropTypes = {
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.elememt),
  ]).isRequired,

  /** If maxLines is defined, will add an <b>ellipsis</b> if string can't fit the last line defined */
  maxLines: PropTypes.number,

  /** Typography has default className to styles, colors, and positions  */
  className: PropTypes.string,
};

const typographyDefaultProps = {
  maxLines: null,
  className: null,
};

/** Typography displays words and characters at various sizes. */
function Typography({ children, maxLines, className, ...rest }) {
  return (
    <Paragraph maxLines={maxLines} className={className} {...rest}>
      {children}
    </Paragraph>
  );
}

Typography.propTypes = typographyPropTypes;

Typography.defaultProps = typographyDefaultProps;

export { Typography, typographyPropTypes, typographyDefaultProps };
