import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const backgroundImagePropTypes = {
  /** Source of an image to be the background, If is null fill the div with the accent color */
  src: PropTypes.string,
  children: PropTypes.node,
};
const backgroundImageDefaultProps = {
  src: null,
  children: null,
};

/** BackgroundImage is a div with background image, so accept children elements */
function BackgroundImage({ src, children, ...rest }) {
  return (
    <Container src={src} {...rest}>
      {children}
    </Container>
  );
}

BackgroundImage.propTypes = backgroundImagePropTypes;

BackgroundImage.defaultProps = backgroundImageDefaultProps;

export {
  BackgroundImage,
  backgroundImagePropTypes,
  backgroundImageDefaultProps,
};
