import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import BroadcastRoomChat from "~/modules/signed/modules/client/pages/BroadcastRoom/components/BroadcastFragment/components/PlayerContainer/BroadcastRoomChat";
import BroadcastRoomPlayer from "~/modules/signed/modules/client/pages/BroadcastRoom/components/BroadcastFragment/components/PlayerContainer/BroadcastRoomPlayer";
import { medias } from "~/shared/styles";

export const Container = styled.div`
  display: flex;
  position: relative;
  border: 1px solid ${props => props.theme.colors.shadow};
  border-radius: 8px;
  max-width: ${props => (props.isChatVisible ? "1366px" : "920px")};
  margin: 0 auto;
  margin-top: 40px;

  @media (max-width: ${medias.mobile}) {
    border: none;
    margin-top: ${props => (props.hasExtraPlayers ? "40px" : 0)}
  }

  //* Styles when chat is off
  ${props =>
    !props.isChatVisible &&
    css`
      .broadcast-room-player {
        #iframe-fragment,
        .plyr--video {
          border-radius: 8px;
        }
      }

      .broadcast-room-chat {
        display: none;
      }
    `}

  //* Styles when theater mode is active
  ${props =>
    props.isTheaterModeActived &&
    css`
      max-width: unset;
      margin: 0;
      border-radius: 0px;
      border: none;
      border-bottom: 1px solid ${props.theme.colors.shadow};
      height: calc(100vh - 80px);

      .broadcast-room-player {
        #iframe-fragment,
        .plyr--video {
          border-radius: unset;
        }
      }

      .broadcast-room-chat {
        border: none;
        border-radius: unset;
        min-width: unset;
        width: 35%;
      }

      #video-controllers {
        left: 0;
        top: 0;
        border-radius: 24px;
        padding: 8px;
        margin: 16px;
        flex-grow: 0;
        transition: all 0.3s;
        opacity: 0.4;

        #video-setting-button {
          cursor: pointer;
          display: flex;
          height: 16px;
          width: 16px;
          color: ${props.theme.colors.accent};
        }

        .video-controller-button {
          display: none;
        }

        &:hover {
          opacity: 1;

          #video-setting-button {
            margin-bottom: 8px;
          }

          .video-controller-button {
            display: flex;
          }
        }
      }
    `}
`;

export const Player = styled(BroadcastRoomPlayer)`
  position: relative;
  width: 100%;

  #iframe-fragment,
  .plyr--video {
    width: 100%;
    height: 100%;
    border-radius: 8px 0 0 8px;
  }

  @media (max-width: ${medias.wide}) {
    #iframe-fragment,
    .plyr--video {
      max-width: ${props => (props.isTheaterModeActived ? "100vw" : "100vw")};
      border-radius: 8px;
    }
  }

  @media (max-width: ${medias.mobile}) {
    #iframe-fragment,
    .plyr--video {
      border-radius: 0 0 8px 8px;
    }
  }
`;

export const Chat = styled(BroadcastRoomChat)`
  border-radius: 0 8px 8px 0;
  width: 40%;
  max-width: 350px;
  min-width: 300px;
  border: none;
  border-left: 1px solid ${props => props.theme.colors.shadow};

  @media (max-width: ${medias.wide}) {
    display: none;
  }
`;
