import types from "./types";

// SIGN IN
export function signInRequest(email, password) {
  return {
    type: types.SIGN_IN.REQUEST,
    payload: { email, password },
  };
}
export function signInSuccess(token, user, email) {
  return {
    type: types.SIGN_IN.SUCCESSFUL,
    payload: { token, user, email },
  };
}
export function signFailure() {
  return {
    type: types.SIGN_IN.FAILED,
  };
}

// SIGN IN APP
export function signInApp({
  email,
  token,
  pathName = "/home/broadcast-rooms",
}) {
  return {
    type: "@auth/SIGN_IN_APP",
    payload: { email, token, pathName },
  };
}

// SIGN IN QRCODE
export function signInQrcode(token, user) {
  return {
    type: "@auth/SIGN_IN_QRCODE",
    payload: { token, user },
  };
}

// SIGN UP
export function signUpRequest(name, email, password) {
  return {
    type: types.SIGN_UP.REQUEST,
    payload: { name, email, password },
  };
}
export function signUpSuccess(user, token) {
  return {
    type: types.SIGN_UP.SUCCESSFUL,
    payload: { user, token },
  };
}
export function signUpFailure() {
  return {
    type: types.SIGN_UP.FAILED,
  };
}

// RESET LOADING
export function resetLoading() {
  return {
    type: "@auth_RESET_LOADING",
  };
}

// RECOVER PASSWORD
export function recoverPassword({ email }) {
  return {
    type: types.RECOVER_PASSWORD.REQUEST,
    payload: { email },
  };
}
export function recoverPasswordSuccessful() {
  return {
    type: types.RECOVER_PASSWORD.SUCCESSFUL,
  };
}

export function recoverPasswordFailed() {
  return {
    type: types.RECOVER_PASSWORD.FAILED,
  };
}

// RESET PASSWORD
export function resetPassword({ token, newPassword, confirmPassword }) {
  return {
    type: types.RESET_PASSWORD.REQUEST,
    payload: { token, newPassword, confirmPassword },
  };
}
export function resetPasswordSuccessful() {
  return {
    type: types.RESET_PASSWORD.SUCCESSFUL,
  };
}

export function resetPasswordFailed() {
  return {
    type: types.RESET_PASSWORD.FAILED,
  };
}

// SIGN OUT
export function signOut(returnPath) {
  return {
    type: "@auth/SIGN_OUT",
    payload: { returnPath },
  };
}
