import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { DialogModal } from "../../components/molecules";

const AlertContext = createContext({});

const AlertProvider = ({ children }) => {
  //* STATES
  const [alert, setAlert] = React.useState(null);
  const [isLoading, setIsLoading] = React.useState(false);

  //* FUNCTIONS
  React.useMemo(() => {
    const { location } = window;

    if (location.state) setAlert(location.state.alert || null);
  }, []);

  const closeAlert = React.useCallback(() => {
    setAlert(null);
  }, []);

  const showAlert = React.useCallback((alertData, disableAutoClose = false) => {
    setAlert({ ...alertData, disableAutoClose });
  }, []);

  const handleConfirmClick = () => {
    if (alert) if (alert.onConfirm) alert.onConfirm();

    if (alert.disableAutoClose === false) closeAlert();
  };

  const startAlertLoading = React.useCallback(() => setIsLoading(true), []);

  const stopAlertLoading = React.useCallback(() => setIsLoading(false), []);

  return (
    <AlertContext.Provider
      value={{ showAlert, closeAlert, startAlertLoading, stopAlertLoading }}
    >
      {children}

      {alert && (
        <DialogModal
          {...alert}
          isLoading={isLoading}
          show
          onConfirm={handleConfirmClick}
          onCancel={closeAlert}
          onClose={closeAlert}
          style={{ maxWidth: 500 }}
        />
      )}
    </AlertContext.Provider>
  );
};

AlertProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

const useAlert = () => {
  const context = useContext(AlertContext);

  if (!context) throw new Error("useAlert must be used within AlertProvider");

  return context;
};

export { AlertProvider, useAlert };
