import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { CellList } from "../../molecules";
import { LogCell, logCellPropTypes } from "../cells/LogCell";
import Loader from "../../molecules/cellLoaders/ActivityCellLoader";

const logPropTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      ...logCellPropTypes,
    })
  ).isRequired,

  isLoading: PropTypes.bool,
};

const logDefaultProps = {
  isLoading: false,
};

/** This components extends <b>CellList</b> props, see all props into <b>Molecules/CellList</b>. */
const Log = ({ data, isLoading, ...rest }) => {
  return (
    <CellList
      {...rest}
      data={data}
      isLoaded={!isLoading}
      cellStyle={{ width: "100%" }}
      scrollStyle={{ width: "100%" }}
      renderCell={(logData, index) => (
        <LogCell
          {...logData}
          noLine={index === data.length - 1}
          style={{
            width: "100%",
            marginBottom: index === data.length - 1 && 40,
          }}
        />
      )}
      renderCellLoader={() => {
        return <Loader style={{ marginBottom: 40 }} />;
      }}
    />
  );
};

Log.propTypes = logPropTypes;

Log.defaultProps = logDefaultProps;

export { Log, logPropTypes, logDefaultProps };
