import React from "react";
import { MdClose, BsBoxArrowUpLeft } from "react-icons/all";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useTranslation } from "../../../hooks/translation";
import { isDataValid } from "../../../utils/validations";
import {
  Card,
  PlayerWrapper,
  Player,
  Content,
  Header,
  PipButton,
} from "./styles";
import { Typography } from "../../atoms";

const pipPropTypes = {
  /** Object containing src (string), type ("youtube" or "vimeo"), currentTime (number) */
  pipData: PropTypes.object.isRequired,
  onClose: PropTypes.func,
  onExpand: PropTypes.func,
};

const pipDefaultProps = {
  onClose: () => {},
  onExpand: () => {},
};

function Pip({ pipData, onClose, onExpand }) {
  const { src, title, subTitle, type, currentTime } = pipData;

  //* STATES
  const [isClosing, setIsClosing] = React.useState(false);
  const [videoController, setVideoController] = React.useState(null);

  // FUNCTIONS
  const { translate } = useTranslation();

  const handleOnClose = () => {
    setIsClosing(true);

    setTimeout(() => {
      setIsClosing(false);
      onClose();
    }, 450);
  };

  const handleOnExpand = () => {
    //* * Get the video currentTime to be autoPlayed */
    const videoCurrentTime =
      isDataValid(videoController) && videoController.currentTime;

    onExpand(videoCurrentTime);
  };

  return (
    <Card isClosing={isClosing}>
      <div id="pip-inner-card">
        <PlayerWrapper isVimeo={type === "vimeo"}>
          <Header id="pip-header">
            <PipButton
              className="pip-button"
              type="button"
              title="Expandir"
              onClick={handleOnExpand}
            >
              <BsBoxArrowUpLeft />
            </PipButton>

            <PipButton
              className="pip-button"
              type="button"
              title={translate("common.close")}
              onClick={handleOnClose}
            >
              <MdClose />
            </PipButton>
          </Header>

          <Player
            src={src}
            type={type}
            plyrId="pip-video-player"
            autoPlay
            currentTime={currentTime}
            setController={setVideoController}
          />

          <div id="pip-backgrop" />
        </PlayerWrapper>

        <Content>
          <Typography className="bold subTitle" maxLines={2}>
            {title}
          </Typography>
          {subTitle && (
            <Typography
              style={{ marginTop: 8 }}
              maxLines={3}
              className="small opaque"
            >
              {subTitle}
            </Typography>
          )}
        </Content>
      </div>
    </Card>
  );
}

Pip.propTypes = pipPropTypes;
Pip.defaultProps = pipDefaultProps;

export { Pip, pipPropTypes, pipDefaultProps };
