import React from "react";
import * as Yup from "yup";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Backdrop,
  HoverWrapper,
  Limiter,
  Icon,
  Box,
  IconButton,
} from "~/shared/components/atoms";
import { Form, Infos } from "~/shared/components/molecules";
import { useSigned } from "~/modules/signed/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import {
  Container,
  Input,
  StickyNote,
  Button,
  Colors,
  Color,
  EmojiPicker,
} from "./styles";

const stickyNotesFormPropTypes = {
  /** Callback that is trigerred when user submit into Form.
   *
   * Return (message, color)
   */
  onSubmit: PropTypes.func.isRequired,
};

const COLORS = ["#CAFE6F", "#FFACDB", "#FDF058", "#FCB951"];

const StickyNotesFormModal = ({ onSubmit, ...rest }) => {
  const { user } = useSigned();
  const { translate } = useTranslation();

  //* STATES
  const [color, setColor] = React.useState("#FDF058");
  const [textValue, setTextValue] = React.useState("");
  const [showEmojis, setShowEmojis] = React.useState(false);

  const isLimitReached = String(textValue).length > 100;

  return (
    <Backdrop {...rest}>
      <Container>
        <Colors>
          {COLORS.map(c => (
            <HoverWrapper key={c} onClick={() => setColor(c)}>
              <Color color={c} isSelected={c === color} />
            </HoverWrapper>
          ))}
        </Colors>

        {showEmojis && (
          <EmojiPicker
            style={{ width: "100%" }}
            className="chat-input-emojis"
            onClick={emoji => setTextValue(textValue + emoji.native)}
          />
        )}

        <StickyNote color={color}>
          <Infos
            avatar={{ src: user.avatar }}
            title={user.name}
            subTitle={translate("entities.stickyNotes.creating")}
          />

          <Form
            id="sticky-note-form"
            onSubmit={data => onSubmit({ ...data, color })}
            yupValidation={{
              message: Yup.string()
                .max(100)
                .required(translate("entities.stickyNotes.emptyError")),
            }}
            style={{ minHeight: "50%" }}
          >
            <Input
              name="message"
              className="sticky-note-message"
              isTextArea
              placeholder={translate("common.writeMessage")}
              maxlength={100}
              maxRows={5}
              value={textValue}
              onChange={e => setTextValue(e.target.value)}
            />
          </Form>

          <Box
            display="flex"
            alignItems="center"
            width="100%"
            justifyContent="flex-end"
          >
            <IconButton
              style={{ marginRight: 10 }}
              onClick={() => setShowEmojis(!showEmojis)}
            >
              <Icon iconName={showEmojis ? "close" : "emojis"} />
            </IconButton>
            <Limiter limit={100} value={String(textValue).length} />
          </Box>

          <Button
            size="small"
            type="submit"
            form="sticky-note-form"
            disabled={isLimitReached}
          >
            Postar
          </Button>
        </StickyNote>
      </Container>
    </Backdrop>
  );
};

StickyNotesFormModal.propTypes = stickyNotesFormPropTypes;

export { StickyNotesFormModal, stickyNotesFormPropTypes };
