/* eslint-disable no-unused-vars */
import React, { useCallback, useRef } from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { Form as Unform } from "@unform/web";

//* CUSTOM IMPORTS
import { getYupErrors } from "../../../utils/forms";
import { isDataValid } from "../../../utils/validations";

const formPropTypes = {
  /** Callback that is triggered when form data pass all validations, return all the
   * form.data
   */
  onSubmit: PropTypes.func,

  /** Callback that is triggered when form do not pass the yupValidations, or when
   * something goes wrong
   */
  onError: PropTypes.func,

  /** An object that contains the <b>Yup</b> validations for the Form */
  yupValidation: PropTypes.object,

  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,

  /** This props will pass the <b>Form</b> ref, for the parent component */
  fowardRef: PropTypes.object,
};

const formDefaultProps = {
  onSubmit: () => {},
  onError: () => {},
  yupValidation: null,
  fowardRef: null,
};

/** Form is commonly used for validate inputs data.
 *
 * Form Component use <b>@unform/web Form component<b>
 * <a href="https://github.com/unform/unform">click here to see more here</a>
 */
function Form({
  onSubmit,
  onError,
  children,
  fowardRef,
  yupValidation,
  ...rest
}) {
  const formRef = useRef(null);

  //* FUNCTIONS
  const handleSubmit = useCallback(
    async data => {
      try {
        //* * Clear errors*/
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const schema = Yup.object().shape(yupValidation);

        await schema.validate(data, {
          abortEarly: false,
        });

        onSubmit(data, formRef.current);
      } catch (err) {
        onError(err);
        //* * Format yup errors*/
        const errors = getYupErrors(err);
        //* * Set errors into form inputs*/
        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [onError, onSubmit, yupValidation]
  );

  return (
    <Unform
      ref={ref => {
        formRef.current = ref;

        if (fowardRef) fowardRef.current = ref;
      }}
      onSubmit={handleSubmit}
      {...rest}
    >
      {children}
    </Unform>
  );
}

Form.propTypes = formPropTypes;
Form.defaultProps = formDefaultProps;

export { Form, formPropTypes, formDefaultProps };
