import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Card as CardC, Typography as TypographyC } from "../../atoms";
import { ExportSheetsButton } from "../ExportSheetsButton";

export const Card = styled(CardC)`
  position: relative;
  padding: 16px 50px;

  ${props =>
    props.accentTheme &&
    css`
      background-color: ${props.theme.colors.accent};

      p {
        color: #fff;
      }
    `}
`;

export const CardDot = styled.div`
  border-radius: 50%;
  height: 8px;
  width: 8px;
  background-color: ${props => props.color};

  position: absolute;
  top: 0;
  left: 0;
  margin-top: 16px;
  margin-left: 16px;
`;

export const ReportItems = styled.div`
  display: ${props => (props.orientation === "row" ? "flex" : "block")};

  .report-item {
    margin-bottom: 20px;
    flex: 1;
  }
`;

export const CounterFieldset = styled.div`
  display: flex;
  align-items: center;

  svg {
    margin-left: 5px;
    height: 16px;
    width: 16px;
    color: ${props => props.theme.colors.accent};
  }
`;

export const Title = styled(TypographyC)`
  margin-bottom: 20px;
  font-size: 12px;
  text-transform: uppercase;
  color: ${props => props.theme.colors.opaque};
  font-weight: bold;
`;

export const Counter = styled(TypographyC)`
  font-size: 26px;
  font-weight: bold;
`;

export const Description = styled(TypographyC)`
  font-size: 12px;
  text-transform: uppercase;
  color: ${props => props.theme.colors.opaque};
  font-weight: bold;
`;

export const DownloadButton = styled(ExportSheetsButton)`
  position: absolute;
  top: 8px;
  right: 0;

  .icon {
    background-color: ${props => props.theme.colors.shadow};
  }

  &:hover {
    .icon {
      background: ${props => props.theme.colors.accent};
    }
  }
`;
