import React from "react";
import PropTypes from "prop-types";
import { MdLock } from "react-icons/md";

//* CUSTOM IMPORTS
import { TagGroup } from "../../../molecules";
import CellLoader from "../../../molecules/cellLoaders/NetworkCellLoader";
import { Typography } from "../../../atoms";
import {
  Touchable,
  Card,
  Tags,
  AdminOptions,
  PrivateOverlay,
  PrivateOverlayIcon,
  Thumbnail,
  CustomButtonContent,
} from "./styles";

const networkRoomCellPropTypes = {
  title: PropTypes.string.isRequired,

  id: PropTypes.number,

  description: PropTypes.string,

  image: PropTypes.string,

  /** Render an list of tags above Thumbnail */
  tags: PropTypes.arrayOf(PropTypes.object),

  /** Render an list of tags below the Card */
  bottomTags: PropTypes.arrayOf(PropTypes.object),

  /** If TRUE, enable <b>CRUDOptionsMenu</b> component, need to implement all crudOptionsModal func */
  isAdmin: PropTypes.bool,

  /** Render a element on the component, default is null */
  customButton: PropTypes.element,

  /** Callback that is triggered when user click into card */
  onClick: PropTypes.func,

  /** If TRUE , add PrivateOverlay and disable cell click */
  isBlocked: PropTypes.bool,

  /** If TRUE , render an Loader instead of the cell */
  isLoading: PropTypes.bool,

  /** If TRUE , change the <b>bookmarkButton</b> color */
  bookmark: PropTypes.bool,

  crudOptionsModal: PropTypes.shape({
    /** Callback that is triggered when user click into <b>CRUDOptionsMenu</b> delete option. */
    onDelete: PropTypes.func,

    /** Callback that is triggered when user click into <b>CRUDOptionsMenu</b> edit option. */
    onEdit: PropTypes.func,

    entity: PropTypes.string.isRequired,

    entityName: PropTypes.string.isRequired,
  }),
};

const networkRoomCellDefaultProps = {
  id: null,
  description: null,
  image: null,
  tags: [],
  bottomTags: [],
  isAdmin: false,
  isBlocked: false,
  onClick: () => {},
  crudOptionsModal: null,
  isLoading: false,
  bookmark: false,
  customButton: null,
};

/** Network room cell  */
const NetworkRoomCell = ({
  title,
  description,
  image,
  tags,
  bottomTags,
  isAdmin,
  crudOptionsModal,
  onClick,
  isBlocked,
  isLoading,
  customButton,
  ...rest
}) => {
  if (isLoading) return <CellLoader />;

  return (
    <Touchable {...rest} isBlocked={isBlocked}>
      {isBlocked && (
        <PrivateOverlayIcon>
          <MdLock />
        </PrivateOverlayIcon>
      )}

      <Card id="network-room-cell-content" onClick={onClick}>
        <Thumbnail src={image}>
          <Tags tags={tags} trim />
        </Thumbnail>

        {isBlocked && <PrivateOverlay />}

        <Typography
          className="subTitle bold"
          maxLines={2}
          style={{ margin: "10px 0" }}
        >
          {title}
        </Typography>

        <Typography className="opaque small" maxLines={4}>
          {description}
        </Typography>
      </Card>

      <TagGroup
        tags={bottomTags}
        borderRadius={4}
        wrap
        style={{ marginTop: 8 }}
      />
      {isAdmin && (
        <AdminOptions
          className="network-room-admin-options"
          {...crudOptionsModal}
        />
      )}

      {customButton && (
        <CustomButtonContent className="network-room-custom-button">
          {customButton}
        </CustomButtonContent>
      )}
    </Touchable>
  );
};

NetworkRoomCell.propTypes = networkRoomCellPropTypes;

NetworkRoomCell.defaultProps = networkRoomCellDefaultProps;

export {
  NetworkRoomCell,
  networkRoomCellDefaultProps,
  networkRoomCellPropTypes,
};
