import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Trans } from "../../atoms";
import { Modal, Content, Line, FooterButtons, Description } from "./styles";

const dialogModalPropTypes = {
  /** If TRUE display modal */
  show: PropTypes.bool,

  /** Callback that is trigerred when user click outside modal */
  onClose: PropTypes.func,

  /** Callback that is trigerred when user click into confirmButton */
  onConfirm: PropTypes.func,

  /** Callback that is trigerred when user click into cancelButtoin */
  onCancel: PropTypes.func,

  /** Modal title */
  title: PropTypes.string,

  /** Modal Description */
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),

  /** If TRUE display an spinner in confirmButton and hide de cancelButton */
  isLoading: PropTypes.bool,

  /** className to customize modal styles */
  className: PropTypes.string,

  /** Control the content inside the confirmButton, default is "confirmar" */
  confirmButton: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),

  /** Control the content inside the cancelButton, default is "cancelar" */
  cancelButton: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),

  /** If TRUE hide the cancelButton */
  onlyConfirm: PropTypes.bool,

  /** Control the variant for this component. Default is 'accent' */
  variant: PropTypes.oneOf(["accent", "error"]),

  /** If <b>true</b> disable the confirm button. */
  isDisable: PropTypes.bool,

  children: PropTypes.element,

  /** If <b>true</b> add an one more click to confirm button onClick be trigerred */
  confirmSecurity: PropTypes.bool,
};

const dialogModalDefaultProps = {
  title: null,
  description: null,
  show: false,
  onClose: () => {},
  onCancel: () => {},
  onConfirm: () => {},
  isLoading: false,
  className: null,
  confirmButton: <Trans translationKey="common.confirm">Confirmar</Trans>,
  cancelButton: <Trans translationKey="common.cancel">Cancelar</Trans>,
  onlyConfirm: false,
  children: null,
  variant: "accent",
  isDisable: false,
  confirmSecurity: false,
};

/** Dialog Modal is commonly used when user need to confirm an action. */
const DialogModal = ({
  title,
  description,
  onConfirm,
  confirmButton,
  cancelButton,
  onlyConfirm,
  onCancel,
  isLoading,
  children,
  variant,
  isDisable,
  confirmSecurity,
  ...rest
}) => {
  return (
    <Modal {...rest} title={title} variant={variant}>
      <Line />

      <Content>
        {description && (
          <Description className="dialog-modal-description">
            {description}
          </Description>
        )}

        {children}
      </Content>

      <FooterButtons
        confirmButtonVariant={variant === "error" && "error"}
        className="dialog-modal-footer-buttons"
        isDisable={isDisable}
        onClick={onConfirm}
        onCancel={onCancel}
        saveButton={confirmButton}
        cancelButton={cancelButton}
        isLoading={isLoading}
        disableCancelButton={onlyConfirm}
        confirmSecurity={confirmSecurity}
      />
    </Modal>
  );
};

DialogModal.propTypes = dialogModalPropTypes;

DialogModal.defaultProps = dialogModalDefaultProps;

export { DialogModal, dialogModalPropTypes, dialogModalDefaultProps };
