import React from "react";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";
import { toast } from "react-toastify";
import PropTypes from "prop-types";
import { validate } from "gerador-validador-cpf";

// ACTIONS
import { listRegistrationFields } from "~/modules/signed/modules/client/store/registrationFields/actions";

// CUSTOM IMPORTS
import { INPUT_TYPES } from "~/shared/utils/inputs";
import { getYupErrors } from "~/shared/utils/forms";
import { isDataValid } from "~/shared/utils/validations";
import { CellList, cellListPropTypes } from "~/shared/components/molecules";
import CellLoader from "~/shared/components/molecules/cellLoaders/InputLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { DynamicField } from "~/modules/signed/modules/client/components/DynamicField";
import { Form } from "./styles";

function DynamicUserForm({
  className,
  formId,
  onSubmit,
  onFailed,
  user,
  renderHeaderComponent,
  footerComponent,
  initialValue,
  noPassword,
  noPasswordValidation,
  cellList,
  cellLoaderWidth,
  noRequiredValidation,
}) {
  const dispatch = useDispatch();
  const { data: registrationFields, isLoaded } = useSelector(
    state => state.registrationFields.list
  );

  // STATE
  const hasUser = isDataValid(user);

  // REFS
  const formRef = React.useRef();

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    dispatch(listRegistrationFields());
  }, [dispatch]);

  const renderInitialData = () => {
    if (initialValue) return initialValue;

    if (hasUser && isLoaded) {
      const { attributes, ...userRest } = user;

      // Fill initial data
      if (isDataValid(attributes)) {
        const formmatedAttrs = {};

        /** Search for fileds that is phone type */
        const phoneAttributes = registrationFields.filter(
          r => r.type === INPUT_TYPES.PHONE.value
        );

        /** If find phone types, get all the data inside the phone field object */
        if (phoneAttributes)
          Object.entries(attributes).forEach(entry => {
            const key = entry[0];
            const value = entry[1];

            /** Find inside the attributes the phone fields type */
            if (phoneAttributes.some(f => f.ref_code === key)) {
              if (value) {
                formmatedAttrs[key] = value.value;
                formmatedAttrs[`${key}-dialCode`] = value.dialCode;
                formmatedAttrs[`${key}-country`] = value.country;
              }
            }
          });

        const userData = {
          ...attributes,
          ...formmatedAttrs,
          ...userRest,
        };

        return userData;
      }

      return user;
    }

    return {};
  };

  const handleSubmit = React.useCallback(
    async data => {
      try {
        const formmatedData = data;
        const phonesData = {};
        const selectedTags = {};

        // Clear errors
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const yupShape = {};

        registrationFields.forEach(r => {
          /** If field is an phone type, get the dialCode and country, and join then
           * into a unique JSON to send to API */
          if (r.type === INPUT_TYPES.PHONE.value) {
            phonesData[r.ref_code] = {
              value: data[r.ref_code],
              dialCode: data[`${r.ref_code}-dialCode`],
              country: data[`${r.ref_code}-country`],
            };

            delete data[`${r.ref_code}-dialCode`];
            delete data[`${r.ref_code}-country`];
          }

          if (r.type === INPUT_TYPES.TAGS.value) {
            const tags = Object.entries(data).find(
              entry => entry[0] === r.ref_code
            );

            const tagsIds = String(tags[1]).split(",");

            selectedTags[r.ref_code] = tagsIds.map(id => Number(id));

            yupShape[r.ref_code] = Yup.string().test(
              "Tags do not has the min selected",
              `${translate("components.dynamicTags.minWarning.part1")} ${
                r.min
              }.`,
              () => {
                if (tags[1]) {
                  if (tagsIds.length < r.min) return false;
                } else if (r.min > 0) return false;

                return true;
              }
            );
          }

          if (noPasswordValidation && r.type === INPUT_TYPES.PASSWORD.value)
            return;

          // Validation in fields Email and Name, now these fields are not required
          if (r.required) {
            if (noRequiredValidation) {
              if (r.ref_code === "email" || r.ref_code === "name")
                yupShape[r.ref_code] = Yup.string().required(
                  translate("handlers.field.isRequired")
                );
            } else {
              yupShape[r.ref_code] = Yup.string().required(
                translate("handlers.field.isRequired")
              );
            }
          }
        });

        const schema = Yup.object().shape(yupShape);

        const cpfValidatior = Yup.object().shape({
          cpf: Yup.string().test(
            "is-cpf-valid",
            translate("handlers.cpf.isInvalid"),
            () => validate(data.cpf)
          ),
        });

        // Call CPF validatior only if CPF field exists
        if (data.cpf)
          await cpfValidatior.validate(
            { cpf: data.cpf },
            {
              abortEarly: false,
            }
          );

        await schema.validate(data, {
          abortEarly: false,
        });

        onSubmit({ ...formmatedData, ...phonesData, ...selectedTags });
      } catch (err) {
        toast.error(translate("handlers.registrationFields.check"));
        onFailed();

        // Format yup errors
        const errors = getYupErrors(err);

        // Set errors into form inputs
        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [
      noPasswordValidation,
      noRequiredValidation,
      onFailed,
      onSubmit,
      registrationFields,
      translate,
    ]
  );

  const renderDynamicField = (registrationField, index) => {
    return (
      <DynamicField
        className="dynamic-input"
        index={index}
        icon={registrationField.icon_type}
        placeholder={registrationField.placeholder}
        title={registrationField.title}
        name={registrationField.ref_code}
        inputType={registrationField.type}
        refCode={registrationField.ref_code}
        isRequired={registrationField.required}
        min={registrationField.min}
        max={registrationField.max}
      />
    );
  };

  return (
    <Form
      id={formId}
      className={className}
      ref={formRef}
      initialData={renderInitialData()}
      onSubmit={handleSubmit}
    >
      <CellList
        id="registration-fields"
        headerComponent={renderHeaderComponent(isLoaded)}
        orientation="column"
        isLoaded={isLoaded}
        data={registrationFields.map(field => {
          if (noPassword && field.type === INPUT_TYPES.PASSWORD.value)
            return null;

          return field;
        })}
        renderCell={(registrationField, index) =>
          renderDynamicField(registrationField, index)
        }
        loaderListSize={5}
        renderCellLoader={() => (
          <CellLoader width={cellLoaderWidth} style={{ width: "100%" }} />
        )}
        gap={16}
        footerComponent={footerComponent}
        {...cellList}
      />
    </Form>
  );
}

DynamicUserForm.propTypes = {
  className: PropTypes.string,
  formId: PropTypes.number,
  onSubmit: PropTypes.func,
  onFailed: PropTypes.func,
  user: PropTypes.object.isRequired,
  renderHeaderComponent: PropTypes.func,
  footerComponent: PropTypes.any,
  initialValue: PropTypes.object,
  noPassword: PropTypes.bool,
  noPasswordValidation: PropTypes.bool,
  cellLoaderWidth: PropTypes.number,
  cellList: PropTypes.shape(cellListPropTypes),
  noRequiredValidation: PropTypes.bool,
};

DynamicUserForm.defaultProps = {
  className: null,
  formId: null,
  onSubmit: () => {},
  onFailed: () => {},
  renderHeaderComponent: () => {},
  footerComponent: null,
  initialValue: null,
  noPassword: false,
  noPasswordValidation: false,
  cellLoaderWidth: 330,
  cellList: null,
  noRequiredValidation: false,
};

export default DynamicUserForm;
