import produce from "immer";

// CUSTOM IMPORTS
import types from "./types";
import { isDataValid } from "~/shared/utils/validations";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isDeleting: null,
    isUpdating: null,
    isCreating: null,
  },
};

export default function broadcastRoomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { tags } = action.payload;

        if (isDataValid(tags)) draft.list.data = tags;

        draft.list.isLoaded = true;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { tag } = action.payload;

        if (isDataValid(tag)) draft.list.data = [...draft.list.data, tag];

        draft.list.isCreating = "FINISH";

        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";

        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { tag } = action.payload;

        if (isDataValid(tag)) {
          const tags = draft.list.data;
          const tagIndex = tags.findIndex(t => t.id === tag.id);

          tags[tagIndex] = tag;

          draft.list.data = tags;
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { tagId } = action.payload;

        const filteredArray = draft.list.data.filter(obj => obj.id !== tagId);

        draft.list.data = filteredArray;

        draft.list.isDeleting = "FINISH";
        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // CLEAR
      case types.CLEAR.CLEAR_TAGS: {
        draft.list.data = [];
        draft.list.isLoaded = false;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isCreating = null;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isLoaded = false;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isCreating = null;

        break;
      }
      default:
    }
  });
}
