import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Icon } from "../../../Icon";
import { Tooltip } from "../../../Tooltip";
import { IconButton } from "../../../IconButton";
import {
  Container,
  TitleContainer,
  InputArea,
  Title,
  IconContainer,
} from "./styles";
import InputLoader from "../../../../molecules/cellLoaders/InputLoader";

const inputContainerPropTypes = {
  /** Render an element at center of <b>InputContainer</b> */
  children: PropTypes.element.isRequired,

  /** Add an title above input */
  title: PropTypes.string,

  /** Add an <b>*</b> in the input title */
  isRequired: PropTypes.bool,

  /** If index exists, will set an zIndex to control overflow layer, when
   * form has many input one above another
   */
  index: PropTypes.number,

  /** Add an icon in the left */
  icon: PropTypes.element,

  variant: PropTypes.oneOf(["error", "filled", "focus", "default", "warning"]),

  /** If this props has some data, render an <b>Tooltip</b> error
   * at rigth of the <b>children</b>
   */
  errorMessage: PropTypes.string,

  /** If this props has some data, render an <b>Tooltip</b> error
   * at rigth of the <b>children</b>
   */
  warningMessage: PropTypes.string,

  /** Render an loader instead of the input */
  isLoading: PropTypes.bool,

  onBlur: PropTypes.func,

  onFocus: PropTypes.func,

  /** Render a component below the Input */
  footerComponent: PropTypes.element,

  /* Tip is information regarding the title  */
  tip: PropTypes.string,

  sizeTip: PropTypes.number,

  tipPosition: PropTypes.oneOf(["right", "top", "botton", "left"]),
};

const inputContainerDefaultProps = {
  title: null,
  isRequired: false,
  index: null,
  errorMessage: null,
  warningMessage: null,
  icon: null,
  variant: "default",
  onBlur: () => {},
  onFocus: () => {},
  isLoading: false,
  footerComponent: null,
  tip: null,
  sizeTip: null,
  tipPosition: null,
};

/** The InputContainer is everything around the input, like the icon, the title
 * and the focus animations, error  */
function InputContainer({
  children,
  title,
  isRequired,
  errorMessage,
  warningMessage,
  icon,
  variant,
  onBlur,
  onFocus,
  index,
  isLoading,
  footerComponent,
  tip,
  tipPosition,
  sizeTip,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);

  return isLoading ? (
    <InputLoader />
  ) : (
    <Container variant={variant} index={index} {...rest}>
      <TitleContainer tip={tip}>
        {title && (
          <Title className="input-container-title">
            {title}
            {isRequired && <span>*</span>}
          </Title>
        )}

        {tip && (
          <IconButton
            tip={tip}
            size={16}
            sizeTip={sizeTip}
            tipPosition={tipPosition}
          >
            <Icon iconName="help" size={14} />
          </IconButton>
        )}
      </TitleContainer>

      <InputArea className="input-container" onBlur={onBlur} onFocus={onFocus}>
        {icon && (
          <IconContainer className="input-container-icon">{icon}</IconContainer>
        )}

        {children}

        {warningMessage && (
          <Tooltip
            className="input-warning"
            message={warningMessage}
            tipColor={colors.yellowWarning}
            icon={{ iconName: "warning", variant: "warning" }}
            style={{ marginRight: errorMessage && 8 }}
          />
        )}

        {errorMessage && (
          <Tooltip
            className="input-error"
            message={errorMessage}
            tipColor={colors.redWrong}
            icon={{ iconName: "error", variant: "error" }}
          />
        )}
      </InputArea>

      {footerComponent}
    </Container>
  );
}

InputContainer.propTypes = inputContainerPropTypes;

InputContainer.defaultProps = inputContainerDefaultProps;

export { InputContainer, inputContainerPropTypes, inputContainerDefaultProps };
