import React from "react";
import PropTypes from "prop-types";
import { MdClose } from "react-icons/md";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { Input } from "../../atoms";
import {
  Container,
  ColorButton,
  ColorPickerContainer,
  ColorPicker,
} from "./styles";
import { useTranslation } from "../../../hooks/translation";

const colorInputPropTypes = {
  /** Name is required to reference the input inside the form  */
  name: PropTypes.string,

  /** Render an Title above ColorInput */
  title: PropTypes.string,

  /** Inital color displayed in ColorInput */
  initialColor: PropTypes.string,
};

const colorInputDefaultProps = {
  name: null,
  title: "",
  initialColor: null,
};

/** ColorInput is commonly used for  get  hexadecimal color via an ColorPicker */
function ColorInput({ name, title, initialColor, ...props }) {
  const { colors } = React.useContext(ThemeContext);

  // STATE
  const [showColorPicker, setShowColorPicker] = React.useState(false);
  const [colorInput, setColorInput] = React.useState(
    initialColor || colors.accent
  );

  // FUNCTIONS
  const { translate } = useTranslation();

  function handleColorChange(event) {
    setColorInput(event.hex);
  }

  function handleCloseColorPicker() {
    setShowColorPicker(false);
  }

  return (
    <Container>
      <Input
        name={name}
        title={title}
        value={colorInput}
        {...props}
        onChange={e => setColorInput(e.target.value)}
      />

      <ColorButton
        color={colorInput}
        onClick={() => setShowColorPicker(true)}
      />

      {showColorPicker && (
        <ColorPickerContainer>
          <button
            type="button"
            onClick={handleCloseColorPicker}
            title={translate("component.colorInput.closePicker")}
          >
            <MdClose />
          </button>
          <ColorPicker color={colorInput} onChange={handleColorChange} />
        </ColorPickerContainer>
      )}
    </Container>
  );
}

ColorInput.propTypes = colorInputPropTypes;

ColorInput.defaultProps = colorInputDefaultProps;

export { ColorInput, colorInputPropTypes, colorInputDefaultProps };
