import api from "~/shared/services/api";
import { updateItem, deleteItem } from "~/shared/utils/lists";

export const listEvents = ({ setIsLoaded, setEvents }) =>
  Promise.resolve(api.get("/admin/event")).then(response => {
    setEvents(response.data.reverse());
    setIsLoaded(true);
  });

export const createEvent = async ({
  eventData,
  events,
  setEvents,
  setIsCreating,
}) => {
  setIsCreating(true);

  await api
    .post("/admin/event", eventData)
    .then(response => {
      const newEvent = response.data;

      setEvents([newEvent, ...events]);

      setIsCreating(false);
    })
    .catch(() => setIsCreating(false));
};

export const updateEvent = async ({
  eventData,
  eventId,
  events,
  setEvents,
  setIsUpdating,
}) => {
  setIsUpdating(true);

  await api
    .put(`/admin/event/${eventId}`, eventData)
    .then(response => {
      const updatedEvent = response.data;

      setEvents(updateItem({ list: events, updatedItem: updatedEvent }));
      setIsUpdating(false);
    })
    .catch(() => setIsUpdating(false));
};

export const deleteEvent = async ({
  eventId,
  setIsDeleting,
  setEvents,
  events,
}) => {
  setIsDeleting(true);

  await api
    .delete(`/admin/event/${eventId}`)
    .then(() => {
      setEvents(deleteItem({ list: events, itemId: eventId }));
      setIsDeleting(false);
    })
    .catch(() => setIsDeleting(false));
};
