import React from "react";
import * as Yup from "yup";
import { Form } from "@unform/web";
import { MdArrowBack, MdLock } from "react-icons/md";
import { useSelector, useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";

//* ACTIONS
import { resetPassword } from "~/modules/auth/store/auth/actions";

//* CUSTOM IMPOSTS
import { useTranslation } from "~/shared/hooks/translation";
import { openSignIn } from "~/shared/routes/navigation";
import logo from "~/shared/assets/custom/logo@2x.png";
import { Button, Input, Trans } from "~/shared/components/atoms";
import { isDataValid } from "~/shared/utils/validations";
import { getYupErrors } from "~/shared/utils/forms";

function ForgotPassword() {
  const location = useLocation();

  //* REDUCER
  const dispatch = useDispatch();
  const { isReseting } = useSelector(state => state.auth);
  const { translate } = useTranslation();

  //* REFS
  const formRef = React.useRef(null);

  //* STATES
  const [token, setToken] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    setToken(urlParams.get("token"));
  }, [location]);

  const handleSubmit = React.useCallback(
    async data => {
      function dispatchRecoverPassword() {
        dispatch(
          resetPassword({
            token,
            confirmPassword: data.passwordConfirmation,
            newPassword: data.password,
          })
        );
      }

      try {
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const schema = Yup.object().shape({
          password: Yup.string().required(
            translate("handlers.newPassword.isRequired")
          ),
          passwordConfirmation: Yup.string().oneOf(
            [Yup.ref("password"), null],
            translate("handlers.passwordConfirmation.isNotEqualsPass")
          ),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        dispatchRecoverPassword();
      } catch (err) {
        const errors = getYupErrors(err);

        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [dispatch, token, translate]
  );

  return (
    <>
      <button className="auth-back-button" type="button" onClick={openSignIn}>
        <MdArrowBack size={16} />
      </button>

      <img src={logo} alt="Logo do evento" className="auth-logo" />

      <Form ref={formRef} onSubmit={handleSubmit}>
        <h3>
          <Trans translationKey="pages.resetPassword.title">
            Redefinir senha
          </Trans>
        </h3>

        <p className="auth-paragraph">
          <Trans translationKey="pages.resetPassword.subTitle">
            Defina uma nova senha para seu acesso.
          </Trans>
        </p>

        <Input
          title={translate("components.input.newPassword.title")}
          name="password"
          type="password"
          placeholder={translate("components.input.newPassword.placeholder")}
          className="auth-input"
          icon={<MdLock />}
        />

        <Input
          title={translate("components.input.passwordConfirmation.title")}
          name="passwordConfirmation"
          type="password"
          placeholder={translate(
            "components.input.passwordConfirmation.placeholder"
          )}
          className="auth-input"
          icon={<MdLock />}
        />

        <Button className="auth-button" type="submit" loading={isReseting}>
          <Trans translationKey="pages.resetPassword.submitButton">
            Redefinir
          </Trans>
        </Button>
      </Form>
    </>
  );
}

export default ForgotPassword;
