import i18n from "../../translation/i18n";

export const USER_TYPES = {
  COMMOM_USER: {
    name: i18n.t("entities.users.userType.name"),
    value: "common_user",
  },
  MODERATOR: {
    name: i18n.t("entities.users.userType.moderator"),
    value: "moderator",
  },
  EDITOR: {
    name: i18n.t("entities.users.userType.editor"),
    value: "editor",
  },
  ADMIN: {
    name: i18n.t("entities.users.userType.administrator"),
    value: "admin",
  },
  SPONSOR: {
    name: i18n.t("entities.users.userType.sponsor"),
    value: "sponsor",
  },
};

export const userTypesOptions = [
  {
    id: 1,
    name: USER_TYPES.COMMOM_USER.name,
    value: USER_TYPES.COMMOM_USER.value,
  },
  {
    id: 2,
    name: USER_TYPES.MODERATOR.name,
    value: USER_TYPES.MODERATOR.value,
  },
  {
    id: 3,
    name: USER_TYPES.EDITOR.name,
    value: USER_TYPES.EDITOR.value,
  },
  {
    id: 4,
    name: USER_TYPES.ADMIN.name,
    value: USER_TYPES.ADMIN.value,
  },
  {
    id: 5,
    name: USER_TYPES.SPONSOR.name,
    value: USER_TYPES.SPONSOR.value,
  },
];

export function convertUserType({ userType }) {
  switch (userType) {
    case USER_TYPES.COMMOM_USER.value:
      return USER_TYPES.COMMOM_USER.name;

    case USER_TYPES.MODERATOR.value:
      return USER_TYPES.MODERATOR.name;

    case USER_TYPES.EDITOR.value:
      return USER_TYPES.EDITOR.name;

    case USER_TYPES.ADMIN.value:
      return USER_TYPES.ADMIN.name;

    case USER_TYPES.SPONSOR.value:
      return USER_TYPES.SPONSOR.name;

    default:
  }

  return null;
}
