import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
    isReordering: false,
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { registrationFields } = action.payload;

        if (isDataValid(registrationFields)) {
          const formattedFields = registrationFields.map(field => {
            return { ...field, id: String(field.id) };
          });

          draft.list.data = formattedFields;
        }

        draft.list.isLoaded = true;

        break;
      }
      case types.LIST.SET: {
        const { registrationFields } = action.payload;

        draft.list.data = registrationFields;

        break;
      }
      case types.LIST.CLEAR: {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { registrationField } = action.payload;

        if (isDataValid(registrationField)) {
          const formattedData = {
            ...registrationField,
            id: String(registrationField.id),
          };

          draft.list.data = [formattedData, ...draft.list.data];
        }

        draft.list.isCreating = "FINISH";
        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { registrationField } = action.payload;

        if (isDataValid(registrationField)) {
          const {
            cadaster_item: cadasterItem,
            dropdown_attributes,
          } = registrationField;

          const auxArray = draft.list.data;
          const index = auxArray.findIndex(
            obj => obj.id === String(cadasterItem.id)
          );

          auxArray[index] = {
            dropdown_attributes,
            ...cadasterItem,
            id: String(cadasterItem.id),
          };

          draft.list.data = auxArray;
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { registrationFieldId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== String(registrationFieldId)
        );

        draft.list.data = filteredArray;
        draft.list.isDeleting = "FINISH";

        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // FORM
      case types.FORM.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isReordering = null;

        break;
      }

      // REORDER
      case types.REORDER.REQUEST: {
        draft.list.isReordering = "PENDING";
        break;
      }
      case types.REORDER.SUCCESSFUL: {
        draft.list.isReordering = "FINISH";
        break;
      }
      case types.REORDER.FAILED: {
        draft.list.isReordering = "FAILED";
        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isReordering = null;
        draft.list.isLoaded = false;

        break;
      }
      default:
    }
  });
}
