import React from "react";
import PropTypes from "prop-types";

// APIs
import {
  createBroadcastRoomRate,
  showBroadcastRoomRate,
} from "~/modules/signed/modules/client/apis/broadcastRooms/rate";

// CUSTOM IMPORTS
import { Stars } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function BroadcastRoomRate({ className, roomId }) {
  const { translate } = useTranslation();

  // STATES
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [rate, setRate] = React.useState(0);

  // FUNCTIONS
  React.useEffect(() => {
    const show = async () => {
      await showBroadcastRoomRate({ roomId }).then(response => {
        setRate(response.data.rate);
        setIsLoaded(true);
      });
    };

    show();
  }, [roomId]);

  const evaluateRoom = async rate => {
    await createBroadcastRoomRate({ rate, roomId });
  };

  return (
    <Stars
      title={translate("common.rateThisSession")}
      className={className}
      initialRate={rate}
      onClick={evaluateRoom}
      isLoaded={isLoaded}
    />
  );
}

BroadcastRoomRate.propTypes = {
  className: PropTypes.string,
  roomId: PropTypes.number.isRequired,
};

BroadcastRoomRate.defaultProps = {
  className: "stars-container",
};

export default BroadcastRoomRate;
