import React, { useEffect, createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIs
import { getVariable } from "~/modules/signed/modules/client/apis/variables";

//* CUSTOM IMPORTS
import { WindowProvider } from "./window";
import { TranslationProvider } from "./translation";
import { ThemeProvider } from "./theme";
import { AlertProvider } from "./alert";
import { NetworkProvider } from "./network";
import { whatsAppButton as whatsAppButtonVariable } from "~/shared/utils/variables";

const AppContext = createContext(null);

const AppProvider = ({ children }) => {
  //* STATES
  const [whatsAppButton, setWhatsAppButton] = React.useState(null);

  //* FUNCTIONS
  useEffect(() => {
    const fetch = async () => {
      await getVariable({
        key: whatsAppButtonVariable.KEY,
      }).then(response => setWhatsAppButton(response.data.value));
    };

    fetch();
  }, []);

  return (
    <AppContext.Provider value={{ whatsAppButton, setWhatsAppButton }}>
      <TranslationProvider>
        <NetworkProvider>
          <ThemeProvider>
            <WindowProvider>
              <AlertProvider>{children}</AlertProvider>
            </WindowProvider>
          </ThemeProvider>
        </NetworkProvider>
      </TranslationProvider>
    </AppContext.Provider>
  );
};

AppProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useApp() {
  const context = useContext(AppContext);

  if (!context) throw new Error("useApp must be used within AppProvider");

  return context;
}

export { AppProvider, useApp };
