import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORT
import { Avatar, Typography, Box, Icon } from "../../atoms";
import { SimpleMenu } from "../../molecules";
import { Table } from "../../molecules/Table";

const rankingTablePropTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      frame: PropTypes.string,
      userName: PropTypes.string,
      points: PropTypes.string,
      position: PropTypes.string,
    })
  ).isRequired,
  /** Callback when user clicks into an option in an row of the table. Return the
   * rankingData value.
   *
   * Example: (ranking) => {}
   */
  onOptionClick: PropTypes.func,
  /** Callback when user clicks into userName or avatar */
  onUserNameClick: PropTypes.func,
  /** options to be used ionside an <b>SimpleMenu</b>. */
  options: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      color: PropTypes.string,
    })
  ),
};

const rankingTableDefaultProps = {
  options: null,
  onOptionClick: () => {},
  onUserNameClick: () => {},
};

const RankingTable = ({
  data,
  onOptionClick,
  onUserNameClick,
  options,
  ...rest
}) => {
  const populateOptions = React.useCallback(() => options, [options]);

  return (
    <Table
      {...rest}
      variant="withLines"
      columnSizes={["75%", "25%"]}
      rows={data.map(ranking => {
        const { avatar, frame, name, rank_position, points } = ranking;

        return {
          user: (
            <Box
              display="flex"
              alignItems="center"
              padding="8px 0"
              marginRight="20px"
            >
              <Typography
                className="accent bold subTitle"
                style={{ wordBreak: "keep-all" }}
              >
                {rank_position}º
              </Typography>

              <Avatar
                src={avatar}
                frame={frame}
                style={{ margin: "0 24px" }}
                onClick={() => onUserNameClick(ranking)}
              />

              <Typography
                className="accent bold subTitle hoverable"
                maxLines={1}
                onClick={() => onUserNameClick(ranking)}
              >
                {name}
              </Typography>
            </Box>
          ),
          points: (
            <Box
              display="flex"
              alignItems="center"
              padding="8px 0"
              justifyContent="space-between"
              width="100%"
            >
              <Typography className="bold">{points} pts</Typography>

              {options && (
                <SimpleMenu
                  fillItems={() => populateOptions()}
                  buttonIcon={<Icon iconName="menu" />}
                  onItemClick={option => onOptionClick(ranking, option)}
                />
              )}
            </Box>
          ),
        };
      })}
    />
  );
};

RankingTable.propTypes = rankingTablePropTypes;

RankingTable.defaultProps = rankingTableDefaultProps;

export { RankingTable, rankingTablePropTypes, rankingTableDefaultProps };
