import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import TitleLoader from "~/shared/components/molecules/cellLoaders/FieldsetLoader";
import { Typography } from "../../atoms";
import { Container } from "./styles";

const importTemplatePropTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string,

  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.element, PropTypes.element),
  ]).isRequired,

  /** Render an Loader instead of the title and subTitle */
  isLoading: PropTypes.bool,
};

const importTemplateDefaultProps = {
  subTitle: null,
  isLoading: false,
};

const ImportTemplate = ({ title, subTitle, children, isLoading }) => {
  return (
    <Container>
      {isLoading ? (
        <TitleLoader style={{ marginBottom: 40 }} />
      ) : (
        <>
          <Typography className="bigTitle" style={{ marginBottom: 8 }}>
            {title}
          </Typography>

          {subTitle && (
            <Typography
              className="subTitle normal"
              style={{ marginBottom: 40 }}
            >
              {subTitle}
            </Typography>
          )}
        </>
      )}

      {children}
    </Container>
  );
};

ImportTemplate.propTypes = importTemplatePropTypes;

ImportTemplate.defaultProps = importTemplateDefaultProps;

export { ImportTemplate, importTemplatePropTypes, importTemplateDefaultProps };
