import React from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  showCertificate,
  clearCertificate,
} from "~/modules/signed/modules/cms/modules/admin/store/certificates/actions";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import CertificateForm from "~/modules/signed/modules/cms/modules/admin/pages/Certificates/pages/components/CertificateForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { Spinner } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

import { openAdminCertificates } from "~/shared/routes/navigation";

function EditCertificate() {
  const { certificateId } = useParams();
  const dispatch = useDispatch();
  const { data: certificate, isLoaded } = useSelector(
    state => state.adminCertificates.object
  );

  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    if (isDataValid(certificateId))
      dispatch(showCertificate({ certificateId }));

    return () => {
      dispatch(clearCertificate());
    };
  }, [certificateId, dispatch]);

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.certificates.plural"),
        function: openAdminCertificates,
      },
      {
        id: 2,
        name: translate("entities.certificates.edit"),
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return isLoaded ? (
    <CertificateForm certificate={certificate} />
  ) : (
    <Spinner centered />
  );
}

export default EditCertificate;
