import api from "~/shared/services/api";

export const createBroadcastRoomRate = ({ rate, roomId }) =>
  Promise.resolve(
    api.post(`/rateroom`, {
      room_id: roomId,
      ref_id: roomId,
      rate,
      type: "room",
    })
  );

export const showBroadcastRoomRate = ({ roomId }) =>
  Promise.resolve(
    api.get(`/rateroom/room`, { params: { room_id: roomId, ref_id: roomId } })
  );
