import produce from "immer";
import scheduleTypes from "./types";
import { isDataValid } from "~/shared/utils/validations";

//* CUSTOM IMPORTS
import { isDateBefore } from "~/shared/utils/dates";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },
};

const sortSchedules = schedules =>
  schedules.sort((a, b) => {
    if (isDateBefore({ date: a.start_time, dateToCompare: b.start_time })) {
      return 1;
    }
    if (!isDateBefore({ date: a.start_time, dateToCompare: b.start_time })) {
      return -1;
    }
    return 0;
  });

export default function adminScheduleReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case "@schedules/LIST_BROADCAST_ROOM_SCHEDULE_SUCCESSFUL": {
        const { schedules } = action.payload;

        if (isDataValid(schedules)) draft.list.data = sortSchedules(schedules);

        draft.list.isLoaded = true;

        break;
      }

      // CREATE
      case scheduleTypes.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";

        break;
      }
      case scheduleTypes.CREATE.SUCCESSFUL: {
        const { schedule } = action.payload;

        if (isDataValid(schedule)) {
          const schedules = draft.list.data;
          schedules.unshift(schedule);

          draft.list.data = sortSchedules(schedules);
        }

        draft.list.isCreating = "FINISH";

        break;
      }
      case scheduleTypes.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";

        break;
      }

      // UPDATE
      case scheduleTypes.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";

        break;
      }
      case scheduleTypes.UPDATE.SUCCESSFUL: {
        const { schedule } = action.payload;

        if (isDataValid(schedule)) {
          const schedules = draft.list.data;
          const scheduleINdex = schedules.findIndex(s => s.id === schedule.id);

          schedules[scheduleINdex] = schedule;

          draft.list.data = sortSchedules(schedules);
        }

        draft.list.isUpdating = "FINISH";

        break;
      }
      case scheduleTypes.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";

        break;
      }

      // DELETE
      case scheduleTypes.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";

        break;
      }
      case scheduleTypes.DELETE.SUCCESSFUL: {
        const { scheduleId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== scheduleId
        );

        draft.list.data = filteredArray;

        draft.list.isDeleting = "FINISH";

        break;
      }
      case scheduleTypes.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";

        break;
      }

      // FORM
      case "@schedules/CLEAR_FORM": {
        draft.list.isCreating = null;
        draft.list.isUpdating = null;
        draft.list.isDeleting = null;

        break;
      }

      case "@schedules/CLEAR": {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isUpdating = null;
        draft.list.isDeleting = null;
        draft.list.isCreating = null;
        draft.list.isLoaded = false;
        break;
      }
      default:
    }
  });
}
