import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import { CellList, Empty } from "~/shared/components/molecules";
import { OutlinedButton, Typography, Icon } from "~/shared/components/atoms";
import CreateFolderModal from "../../../components/CreateFolderModal";
import {
  FolderMaterialCell,
  DownloadMaterialCell,
} from "~/shared/components/organisms/cells";
import { useDownloadMaterial } from "../../../hooks";

import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { Container, Header, AddFolderButton } from "./styles";

import {
  openFolderEditMaterials,
  openFolderCreateMaterials,
  openFolderDetails,
} from "~/shared/routes/navigation";

import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function FolderMaterialsList() {
  const { folderId } = useParams();

  const {
    materials,
    folders,
    isLoaded,
    createMaterialFolder,
    editFolder,
    deleteMaterial,
    deleteMaterialFolder,
    listFolders,
    listMaterial,
    setMaterialFolder,
  } = useDownloadMaterial();

  const [editMode, setEditMode] = React.useState(false);
  const [showModal, setShowModal] = React.useState(false);
  const [folderData, setFolderData] = React.useState(null);

  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  React.useEffect(() => {
    listFolders(folderId);
    listMaterial(folderId);

    return () => {
      setMaterialFolder(null);
    };
  }, [listFolders, listMaterial, folderId, setMaterialFolder]);

  const openModal = () => {
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);

    setEditMode(false);

    setMaterialFolder(null);
  };

  const handleCreateFolder = () => {
    openModal();

    setEditMode(false);
  };

  const handleEditFolder = cell => {
    openModal();

    setFolderData(cell);

    setEditMode(true);
  };

  const renderAddButton = () => (
    <OutlinedButton
      title={translate(
        "entities.downloadMaterials.downloadMaterialList.addMaterial.title"
      )}
      placeholder={translate(
        "entities.downloadMaterials.downloadMaterialList.addMaterial.placeholder"
      )}
      style={{
        minWidth: 302,
        maxWidth: 300,
        height: 243,
        margin: 0,
      }}
      onClick={() => openFolderCreateMaterials(folderId)}
    />
  );

  const renderCellMaterials = material => (
    <DownloadMaterialCell
      isAdmin
      title={dTranslate(material.title)}
      fileSize={material.file_size}
      url={material.file}
      fileType={material.file_type}
      thumbnail={material.thumbnail}
      onEdit={() => openFolderEditMaterials(folderId, material.id)}
      onDelete={() => deleteMaterial(material.id)}
    />
  );

  const renderCellFolder = folder => (
    <FolderMaterialCell
      isAdmin
      title={dTranslate(folder.title)}
      onClick={() => openFolderDetails(folder.id)}
      onEdit={() => handleEditFolder(folder)}
      onDelete={() => deleteMaterialFolder(folder.id)}
    />
  );

  const isEmpty = isLoaded && materials.length === 0;

  return (
    <Container>
      <Header>
        <AddFolderButton type="button" onClick={handleCreateFolder}>
          {translate(
            "entities.downloadMaterials.downloadMaterialList.addFolder"
          )}
        </AddFolderButton>
      </Header>
      {folders.length !== 0 && (
        <CellList
          headerComponent={
            <Typography
              className="subTitle accent bold"
              style={{
                margin: "0 0 10px",
              }}
            >
              {translate(
                "entities.downloadMaterials.downloadMaterialList.folders"
              )}
            </Typography>
          }
          data={folders}
          renderCell={renderCellFolder}
          loaderListSize={9}
          gap={20}
          renderCellLoader={() => (
            <CellLoader width={300} height={243} style={{ height: 243 }} />
          )}
          cellStyle={{ marginBottom: 20 }}
          isLoaded={isLoaded}
        />
      )}

      <CellList
        headerComponent={
          <Typography
            className="subTitle accent bold"
            style={{
              margin: "0 0 10px",
            }}
          >
            {translate(
              "entities.downloadMaterials.downloadMaterialList.materials"
            )}
          </Typography>
        }
        data={materials}
        firstCell={!isEmpty && renderAddButton()}
        renderCell={renderCellMaterials}
        loaderListSize={9}
        gap={20}
        renderCellLoader={() => (
          <CellLoader width={300} height={243} style={{ height: 243 }} />
        )}
        isLoaded={isLoaded}
        emptyComponent={
          <Empty
            style={{
              position: "relative",
              bottom: 0,
              alignItems: "flex-start",
            }}
            message={translate("entities.downloadMaterials.emptyList.title")}
            description={translate(
              "entities.downloadMaterials.emptyList.description"
            )}
            button={{
              onClick: () => openFolderCreateMaterials(folderId),
              children: translate(
                "entities.downloadMaterials.downloadMaterialList.addMaterial.placeholder"
              ),
              icon: <Icon iconName="download" color="#fff" />,
            }}
          />
        }
      />

      <CreateFolderModal
        show={showModal}
        folder={folderData}
        onClose={closeModal}
        onCreate={createMaterialFolder}
        onEdit={editFolder}
        editMode={editMode}
      />
    </Container>
  );
}

export default FolderMaterialsList;
