import React from "react";
import PropTypes from "prop-types";

//* APIs
import { showBroadcastRoomScheduleRate } from "~/modules/signed/modules/client/apis/broadcastRooms/schedule/rate";

//* CUSTOM IMPORTS
import { getFullHours, isDateAfter, isDateBefore } from "~/shared/utils/dates";
import { Box, Typography } from "~/shared/components/atoms";
import { ScheduleCell } from "~/shared/components/organisms/cells";
import { UserScheduleCell } from "~/modules/signed/modules/client/components/cells/UserScheduleCell";
import EvaluateModal from "~/modules/signed/modules/client/components/modals/ScheduleDetailsModal";
import { BookmarkButton } from "../../buttons/BookmarkButton";

const scheduleViewListPropTypes = {
  liveSchedule: PropTypes.arrayOf(PropTypes.object).isRequired,
  schedule: PropTypes.arrayOf(PropTypes.object).isRequired,
  className: PropTypes.string,
  title: PropTypes.string,
  roomId: PropTypes.number.isRequired,
  onEvaluate: PropTypes.func,
};

const scheduleViewListDefaultProps = {
  className: "schedule-view-list",
  title: null,
  onEvaluate: () => {},
};

const LiveScheduleCell = ({ schedule, onEvaluate, onChangedToNotLive }) => {
  const {
    title,
    description,
    id,
    bookmark,
    speakers,
    start_time: startTime,
    end_time: endTime,
  } = schedule;
  const [initialRate, setInitialRate] = React.useState(0);
  const [isLoaded, setIsLoaded] = React.useState(false);

  React.useEffect(() => {
    const fetch = async () => {
      await showBroadcastRoomScheduleRate({ scheduleId: id }).then(response => {
        setInitialRate(response.data.rate);
        setIsLoaded(true);
      });
    };

    fetch();
  }, [id]);

  const renderVariant = React.useCallback(
    s => {
      const now = new Date();

      if (isDateAfter({ date: endTime, dateToCompare: now }))
        onChangedToNotLive(s);

      return "live";
    },
    [endTime, onChangedToNotLive]
  );

  return (
    <UserScheduleCell
      key={id}
      id={id}
      customButton={
        <BookmarkButton
          isSelected={bookmark}
          apiType="schedules"
          apiId={id}
          variant="icon"
        />
      }
      disableHoverEffect
      disableMenu
      title={title}
      description={description}
      speakers={speakers}
      startTime={getFullHours(startTime)}
      endTime={getFullHours(endTime)}
      stars={{
        initialRate,
        onClick: rate => onEvaluate(rate, id),
        isLoaded,
      }}
      style={{ marginBottom: 10 }}
      variant={renderVariant(schedule)}
    />
  );
};

LiveScheduleCell.propTypes = {
  schedule: PropTypes.object.isRequired,
  onEvaluate: PropTypes.func.isRequired,
  onChangedToNotLive: PropTypes.func.isRequired,
};

const ScheduleViewList = ({
  className,
  roomId,
  liveSchedule,
  schedule,
  title,
  onEvaluate,
}) => {
  //* STATE
  const [liveSchedules, setLiveSchedules] = React.useState(liveSchedule);
  const [schedules, setSchedules] = React.useState(schedule);
  const [showEvaluateModal, setEvaluateModal] = React.useState(false);
  const [currentSchedule, setCurrentSchedule] = React.useState(null);

  //* FUNCTIONS
  function onCellClick(s) {
    setCurrentSchedule(s);
    setEvaluateModal(true);
  }

  const addScheduleToLive = React.useCallback(s => {
    setLiveSchedules(list => [...list, s]);

    setSchedules(list => list.filter(obj => obj.id !== s.id));
  }, []);

  const removeScheduleToLive = React.useCallback(s => {
    setSchedules(list => [s, ...list]);

    setLiveSchedules(list => list.filter(obj => obj.id !== s.id));
  }, []);

  const renderScheduleCellVariant = React.useCallback(
    s => {
      if (!s) return null;

      const { start_time: startTime, end_time: endTime } = s;

      const now = new Date();

      /** If schedule is in live change the ScheduleCell variant to 'live' */
      if (
        isDateAfter({ date: startTime, dateToCompare: now }) &&
        isDateBefore({ date: endTime, dateToCompare: now })
      )
        addScheduleToLive(s);

      /** If user select an ScheduleCell add an visual to the selected cell */
      if (currentSchedule) if (currentSchedule.id === s.id) return "accent";

      return "default";
    },
    [addScheduleToLive, currentSchedule]
  );

  return (
    <>
      <Box className={className}>
        {title && (
          <Typography className="title bold" style={{ marginBottom: 8 }}>
            {title}
          </Typography>
        )}

        {liveSchedules.length > 0 && (
          <Box>
            <Typography className="opaque" style={{ marginBottom: 8 }}>
              Ao vivo neste momento
            </Typography>

            {liveSchedules.map(s => {
              return (
                <LiveScheduleCell
                  schedule={s}
                  onEvaluate={onEvaluate}
                  roomId={roomId}
                  onChangedToNotLive={removeScheduleToLive}
                />
              );
            })}
          </Box>
        )}

        {schedules.length > 0 && (
          <Box>
            <Typography className="opaque" style={{ marginBottom: 8 }}>
              Toda programação
            </Typography>

            {schedules.map(s => {
              const {
                title: scheduleTitle,
                id,
                speakers,
                start_time,
                end_time,
                bookmark,
              } = s;

              return (
                <ScheduleCell
                  key={id}
                  id={id}
                  customButton={
                    <BookmarkButton
                      isSelected={bookmark}
                      apiType="schedules"
                      apiId={id}
                      variant="icon"
                    />
                  }
                  disableMenu
                  title={scheduleTitle}
                  speakers={speakers}
                  startTime={getFullHours(start_time)}
                  endTime={getFullHours(end_time)}
                  onClick={() => onCellClick(s)}
                  style={{ marginBottom: 10 }}
                  variant={renderScheduleCellVariant(s)}
                />
              );
            })}
          </Box>
        )}
      </Box>

      {showEvaluateModal && (
        <EvaluateModal
          schedule={currentSchedule}
          onClose={() => {
            setEvaluateModal(false);
            setCurrentSchedule(null);
          }}
          roomId={roomId}
        />
      )}
    </>
  );
};

ScheduleViewList.propTypes = scheduleViewListPropTypes;

ScheduleViewList.defaultProps = scheduleViewListDefaultProps;

export default ScheduleViewList;
