import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Span } from "./styles";

const countryIconPropTypes = {
  /** This props define which flag will be rendered,
   *
   * you can see all countryCodes <a target="blank" href="https://www.iso.org/obp/ui/#search/code/">here</a>
   */
  countryCode: PropTypes.string,

  /** The size in <b.px</b> for this component, default size is <b>24</b> */
  size: PropTypes.number,
};

const countryIconDefaultProps = {
  countryCode: "br",
  size: 18,
};

/** CountryIcon use <b>flag-icon-css lib</b> to render the country flags.
 *
 * Click <a href="https://github.com/lipis/flag-icon-css">here</a> to see all documentation
 */
const CountryIcon = ({ countryCode, size, ...rest }) => {
  const borderRadius = size * 0.2;
  const width = size * 1.3333;
  const height = size;

  return (
    <Span
      {...rest}
      className={`flag-icon flag-icon-${countryCode}`}
      width={width}
      height={height}
      borderRadius={borderRadius}
    />
  );
};

CountryIcon.propTypes = countryIconPropTypes;
CountryIcon.defaultProps = countryIconDefaultProps;

export { CountryIcon, countryIconPropTypes, countryIconDefaultProps };
