import React from "react";
import PropTypes from "prop-types";

//* APIs
import { listUserImportsLog } from "../../../../../../apis/users";

//* CUSTOM IMPORTS
import { useAlert } from "~/shared/hooks/alert";
import { useCMS } from "../../../../../../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";

import { getFullDateBR, getFullHours } from "~/shared/utils/dates";
import { TABS_INDEX, useImportUsers } from "../../hooks";
import { IMPORT_STATUS } from "../../../../../../hooks/webSocket/channels/import";
import { ImportTemplate } from "~/shared/components/templates";
import {
  Dropzone,
  Table,
  Pagination,
  SearchBar,
} from "~/shared/components/molecules";
import { sheetToJson } from "~/shared/utils/sheets";
import {
  openAdminUserImportMatch,
  openAdminUserImportStatus,
} from "~/shared/routes/navigation";
import {
  Typography,
  LinkButton,
  CircularProgress,
  Icon,
  Box,
  IconButton,
} from "~/shared/components/atoms";

const ImportUser = ({ onReset, isCleaning }) => {
  const { translate } = useTranslation();
  const { showAlert } = useAlert();
  const { setFooterButtons } = useCMS();
  const {
    receiveSheetData,
    sheetData,
    clearData,
    tabIndex,
    setSheetFile,
    importLog,
    setImportLog,
    importLogPagination,
    setImportLogPagination,
  } = useImportUsers();

  //* STATES
  const [error, setError] = React.useState(null);
  const [isUploading, setIsUploading] = React.useState(false);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      setIsLoading(true);

      await listUserImportsLog({ page: 1, search }).then(response => {
        const { page, lastPage, data } = response.data;

        setImportLog(data);
        setIsLoaded(true);
        setImportLogPagination({
          page,
          lastPage,
        });
      });

      setIsLoading(false);
    };

    list();
  }, [search, setImportLog, setImportLogPagination]);

  React.useEffect(() => {
    if (tabIndex === TABS_INDEX.IMPORT && sheetData.length)
      setFooterButtons({
        isVisible: true,
        saveButton: translate("common.advance"),
        disableCancelButton: true,
        onClick: openAdminUserImportMatch,
      });
  }, [setFooterButtons, sheetData, tabIndex, translate]);

  const verifySheet = files => {
    setIsUploading(true);

    //* *Remove the error if has one*/
    if (error) setError(null);

    files.forEach(file => {
      setSheetFile(file);

      sheetToJson({ sheet: file, maxLines: 10000 })
        .then(response => {
          const { headers, jsonArray } = response.data;

          receiveSheetData(jsonArray, headers);

          setIsUploading(false);

          openAdminUserImportMatch();
        })
        .catch(err => {
          setError(err);
          setIsUploading(false);
        });
    });
  };

  const handleResetImport = files => {
    //* *Clear all data inside import user hook */
    clearData();
    verifySheet(files);
    onReset();
  };

  const handleOnDropAccepted = files => {
    let cancelDrop = false;

    //* *If Dropzone already has an sheet loaded show an alert */
    if (sheetData.length) {
      cancelDrop = true;

      showAlert({
        title: "Tem certeza que deseja continuar?",
        description: "Você perderá todos seu progresso!",
        confirmButton: translate("common.continue"),
        onConfirm: () => {
          handleResetImport(files);
          cancelDrop = false;
        },
      });
    }

    if (!cancelDrop) verifySheet(files);
  };

  const handlePagination = async p => {
    setIsLoading(true);

    setImportLogPagination({ ...importLogPagination, page: p });

    await listUserImportsLog({ page: p, search }).then(response => {
      const { data } = response.data;

      setImportLog(data);
    });

    setIsLoading(false);
  };

  return (
    <ImportTemplate
      title={translate("entities.users.uploadYourUsers")}
      subTitle={translate("common.selectSpreadsheet")}
    >
      {!isCleaning && (
        <Dropzone
          variant={!!error && "error"}
          errorMessage={error}
          isLoading={isUploading}
          loadingMessage={`${translate(
            "common.validatingSpreadsheetInformations"
          )}...`}
          placeholder={translate("common.selectOrDragSpreadsheetHere")}
          acceptedFormats=".xlsx, .csv"
          onDropAccepted={handleOnDropAccepted}
        />
      )}

      <Box
        display="flex"
        flexDirection="column"
        alignItens="center"
        justifyContent="center"
        maxWidth="625px"
        marginBottom="30px"
      >
        <Box display="flex" justifyContent="space-between">
          <Typography className="bigTitle normal">
            {translate("common.lastImports")}
          </Typography>

          <SearchBar
            style={{ width: "40%", marginBottom: 20 }}
            placeholder={translate("common.searchSpreadsheet")}
            onChange={value => setSearch(value)}
          />
        </Box>

        <Table
          empty={{
            variant: search && "search",
            search,
            message: translate("common.withoutImports"),
            description: translate("common.NoImportHasBeenPerformedSoFar"),
            icon: <Icon iconName="download" />,
          }}
          loaderListSize={5}
          isLoading={!isLoaded || isLoading}
          containerVariant="contained"
          variant="withLines"
          size="regular"
          titles={[
            translate("common.fileName"),
            translate("common.ImportCarriedOutIn"),
            translate("common.progress"),
          ]}
          rows={importLog.map(lastImport => {
            const {
              id,
              file,
              status,
              file_name: fileName,
              created_at: createdAt,
            } = lastImport;

            return {
              fileName: (
                <LinkButton
                  onClick={() => openAdminUserImportStatus({ importId: id })}
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    marginRight: 16,
                    whiteSpace: "nowrap",
                  }}
                >
                  {fileName}
                </LinkButton>
              ),
              createdAt: (
                <Typography>
                  {`${getFullDateBR(createdAt)} - ${getFullHours(createdAt)}`}
                </Typography>
              ),
              progress: (
                <Box display="flex" marginLeft="12px">
                  <CircularProgress
                    progress={status === IMPORT_STATUS.FINISHED && 100}
                    icon={
                      status === IMPORT_STATUS.STARTED && (
                        <Icon iconName="time" variant="accent" />
                      )
                    }
                    size={40}
                  />

                  <IconButton
                    title={translate("common.downloadSpreadsheet")}
                    style={{ marginLeft: 50 }}
                    onClick={() => window.open(file)}
                  >
                    <Icon iconName="download" />
                  </IconButton>
                </Box>
              ),
            };
          })}
        />

        {isLoaded && !isLoading && (
          <Pagination
            style={{ marginTop: 16 }}
            handlePagination={page => handlePagination(page)}
            currentPage={importLogPagination.page}
            lastPage={importLogPagination.lastPage}
          />
        )}
      </Box>
    </ImportTemplate>
  );
};

ImportUser.propTypes = {
  onReset: PropTypes.func.isRequired,
  isCleaning: PropTypes.bool.isRequired,
};

export default ImportUser;
