import React from "react";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";
import PropTypes from "prop-types";

const SingleBarLoader = ({ width, height, ...rest }) => {
  const { colors } = React.useContext(ThemeContext);

  return (
    <ContentLoader
      speed={2}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
      {...rest}
    >
      <rect x="0" y="0" rx="8" ry="8" width={width} height={height} />
    </ContentLoader>
  );
};

SingleBarLoader.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
};

SingleBarLoader.defaultProps = {
  width: 200,
  height: 16,
};

export default SingleBarLoader;
