import React from "react";
import PropTypes from "prop-types";
import { MdKeyboardArrowDown } from "react-icons/md";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Box, Avatar, avatarPropTypes } from "../../atoms";
import { SimpleMenu, simpleMenuPropTypes } from "..";

const avatarMenuPropTypes = {
  /** object containing src and size */
  avatar: PropTypes.shape(avatarPropTypes).isRequired,
  /** fillitens props from simpleMenu */
  simpleMenu: PropTypes.shape(simpleMenuPropTypes).isRequired,
};

function AvatarMenu({ avatar, simpleMenu, ...rest }) {
  const { header } = React.useContext(ThemeContext);

  return (
    <Box {...rest} display="flex" alignItens="center">
      <Avatar {...avatar} />

      <SimpleMenu
        buttonIcon={<MdKeyboardArrowDown size={20} color={header.text} />}
        {...simpleMenu}
      />
    </Box>
  );
}

AvatarMenu.propTypes = avatarMenuPropTypes;

export { AvatarMenu, avatarMenuPropTypes };
