import React from "react";
import { useNotificationSocket } from "~/modules/signed/hooks/webSocket/channels/notification";
import {
  getNotifications,
  deleteNotification,
  readAllNotifications,
} from "~/modules/signed/modules/client/apis/notifications";

//* CUSTOM IMPORTS
import { NotificationButton } from "~/shared/components/organisms";
import { pushState } from "~/shared/services/history";
import {
  getDistanceToNow,
  getFullDateBR,
  getFullHours,
  isToday,
} from "~/shared/utils/dates";
import { isTheSameDomain } from "~/shared/utils/strings";
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { clientPaths } from "~/modules/signed/modules/client/routes/paths";

const ClientNotificationButton = () => {
  //* SOCKET
  const { newNotification, emitNotificationOpened } = useNotificationSocket();

  //* STATES
  const [isNotificationsLoaded, setIsNotificationLoaded] = React.useState(
    false
  );
  const [notificationData, setNotificationData] = React.useState([]);
  const [page, setPage] = React.useState(1);
  const [lastPage, setLastPage] = React.useState();
  const [isPaging, setIsPaging] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      await getNotifications({ page: 1 }).then(response => {
        setNotificationData(response.data.data);
        setPage(response.data.page);
        setLastPage(response.data.lastPage);
        setIsNotificationLoaded(true);
      });
    };

    fetch();
  }, []);

  const handleNotificationsPaginate = async () => {
    if (page >= lastPage) return;

    setIsPaging(true);

    await getNotifications({ page: page + 1 }).then(response => {
      setNotificationData([...notificationData, ...response.data.data]);
      setPage(response.data.page);
      setLastPage(response.data.lastPage);
    });

    setIsPaging(false);
  };

  const hasLinkVerify = notification => {
    if (notification.link) {
      return true;
    }
    return false;
  };

  React.useMemo(
    () =>
      newNotification &&
      setNotificationData(list => [
        { ...newNotification, created_at: new Date() },
        ...list,
      ]),
    [newNotification]
  );

  const notifications = React.useMemo(
    () =>
      notificationData.map(notification => {
        const {
          has_received,
          has_opened,
          created_at: createdAt,
        } = notification;
        if (has_opened) {
          return {
            ...notification,
            isReaded: true,
            hasLink: hasLinkVerify(notification),
            time: isToday({ date: createdAt })
              ? getDistanceToNow(createdAt)
              : `${getFullHours(createdAt)}-${getFullDateBR(createdAt)}`,
          };
        }
        switch (notification.type) {
          case NOTIFICATION_TYPES.CONNECTION:
            return {
              ...notification,
              hasLink: true,
              link: `${clientPaths.profile.index}/${notification.user_connection_id}`,
              icon: "userAdd",
              isReaded: has_received,
              time: isToday({ date: createdAt })
                ? getDistanceToNow(createdAt)
                : `${getFullHours(createdAt)}-${getFullDateBR(createdAt)}`,
            };

          case NOTIFICATION_TYPES.GAMIFICATION:
            return {
              ...notification,
              icon: "trophy",
              isReaded: has_received,
              time: isToday({ date: createdAt })
                ? getDistanceToNow(createdAt)
                : `${getFullHours(createdAt)}-${getFullDateBR(createdAt)}`,
            };

          default:
            return {
              ...notification,
              hasLink: hasLinkVerify(notification),
              isReaded: has_received,
              time: isToday({ date: createdAt })
                ? getDistanceToNow(createdAt)
                : `${getFullHours(createdAt)}-${getFullDateBR(createdAt)}`,
            };
        }
      }),
    [notificationData]
  );

  /** Deals with all notificaiton types on link click */
  const handleOnNotificationLinkClick = notification => {
    if (!notification) return;

    const { type, payload, link } = notification;

    switch (type) {
      case NOTIFICATION_TYPES.CONNECTION:
        pushState(
          `${clientPaths.profile.index}/${payload.connection.source_connection_id}`
        );
        break;

      default:
        emitNotificationOpened(notification.id);

        if (isTheSameDomain(link)) {
          const domain = new URL(link);

          pushState(domain.pathname);
        } else {
          window.open(link);
        }
        break;
    }
  };

  const handleDeleteNotification = async notification => {
    if (!notification) return;
    setNotificationData(notificationData.filter(n => n.id !== notification.id));
    await deleteNotification({ id: notification.id });
  };

  const hasNewVerify = () => {
    return notificationData.some(
      notification => !notification.has_received && !notification.has_opened
    );
  };

  const handleOnReadAllNotifications = async () => {
    setNotificationData(
      notificationData.map(notification => ({
        ...notification,
        has_received: true,
      }))
    );

    if (hasNewVerify()) await readAllNotifications({ action: "received" });
  };

  return (
    <NotificationButton
      data={notifications}
      onDelete={handleDeleteNotification}
      onBottomReached={handleNotificationsPaginate}
      isPaging={isPaging}
      onReadAll={handleOnReadAllNotifications}
      onLinkClick={notification => handleOnNotificationLinkClick(notification)}
      isLoaded={isNotificationsLoaded}
      hasNew={hasNewVerify()}
      style={{ marginLeft: 8 }}
    />
  );
};

export { ClientNotificationButton };
