import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";

// ACTIONS
import {
  createExtraPlayer,
  updateExtraPlayer,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import history from "~/shared/services/history";
import {
  Modal,
  Title,
  Form,
  TranslateInput,
  PlayerInput,
  Footer,
} from "./styles";
import Icons from "~/modules/signed/modules/cms/components/lists/IconList";
import { useTranslation } from "~/shared/hooks/translation";

function ExtraPlayerModal({ extraPlayer, refType, refId }) {
  const { translate } = useTranslation();

  // REDUCER
  const dispatch = useDispatch();

  const { isCreating, isUpdating } = useSelector(
    state => state.adminExtraLinks.extraPlayers
  );

  // STATES
  const [show, setShow] = React.useState(true);
  const [formData, setFormData] = React.useState({});
  const [selectedIcon, setSelectedIcon] = React.useState("");

  const editMode = isDataValid(extraPlayer);
  const extraButtonId = editMode ? extraPlayer.id : null;

  // FUNCTIONS
  React.useEffect(() => {
    if (editMode) {
      setFormData(extraPlayer);
      setSelectedIcon(extraPlayer.type);
    }
  }, [editMode, extraPlayer]);

  const handleOnClose = () => {
    setShow(false);

    setTimeout(() => history.goBack(), 300);
  };

  const handleSubmit = data => {
    const extraButtonData = {
      ...data,
      ref_id: refId,
      ref_type: refType,
      order: 0,
      type: selectedIcon,
      videoType: data.type,
    };

    if (!editMode) {
      dispatch(createExtraPlayer(extraButtonData));
    } else {
      dispatch(updateExtraPlayer(extraButtonData, extraButtonId));
    }
  };

  return (
    <Modal show={show} onClose={handleOnClose} withScroll>
      <Form
        onSubmit={data => handleSubmit(data)}
        yupValidation={{
          label: Yup.string().required(
            translate("entities.broadcastRooms.warnings.nameBroadcast")
          ),
          url: Yup.string().required(
            translate("entities.broadcastRooms.warnings.linkBroadcast")
          ),
        }}
        initialData={formData}
      >
        <Title>
          {editMode
            ? translate("entities.broadcastRooms.accessibilityTabTitleExtra")
            : translate(
                "entities.broadcastRooms.accessibilityTabButtonLabelExtra"
              )}
        </Title>

        <TranslateInput
          name="label"
          title={translate("entities.broadcastRooms.modalBroadcast.name")}
          placeholder={translate(
            "entities.broadcastRooms.modalBroadcast.placeholderName"
          )}
        />

        <PlayerInput
          name="url"
          placeholder={translate(
            "entities.broadcastRooms.modalBroadcast.placeholderInput"
          )}
          src={editMode ? extraPlayer.url : null}
          type={editMode ? extraPlayer.videoType : "vimeo"}
        />

        <Icons setSelectedIcon={setSelectedIcon} selectedIcon={selectedIcon} />

        <Footer
          onCancel={handleOnClose}
          isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
        />
      </Form>
    </Modal>
  );
}

ExtraPlayerModal.propTypes = {
  extraPlayer: PropTypes.object,
  refType: PropTypes.string.isRequired,
  refId: PropTypes.number.isRequired,
};

ExtraPlayerModal.defaultProps = {
  extraPlayer: null,
};

export default ExtraPlayerModal;
