import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useSponsorCMS } from "../../../../hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { InfosSelect, SlapHole } from "./styles";
import { useAlert } from "~/shared/hooks/alert";
import { Box } from "~/shared/components/atoms";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import Loader from "~/shared/components/molecules/cellLoaders/CardLoader";

const cmsInfosSelectPropTypes = {
  onItemClick: PropTypes.func.isRequired,
};

const SponsorInfosSelect = ({ ...rest }) => {
  const {
    managedPartner,
    changeManagedPartner,
    managedPartners,
  } = useSponsorCMS();
  const { translate } = useTranslation();
  const { dTranslate, toJsonTranslation } = useDynamicTranslation();
  const { showAlert } = useAlert();

  //* STATES
  const [isOpened, setIsOpened] = React.useState(false);

  //* FUNCTIONS
  const handleOnItemClick = partner => {
    setIsOpened(false);

    changeManagedPartner({
      ...partner,
      title: toJsonTranslation(partner.subTitle),
    });
  };

  const handleShow = partner => {
    showAlert({
      title: translate("entities.sponsors.changeAlert.title"),
      description: (
        <>
          {translate("entities.sponsors.changeAlert.description")}
          <strong>{partner.subTitle}.</strong>
          {translate("common.doYouWishToContinue")}
        </>
      ),
      onConfirm: () => handleOnItemClick(partner),
    });
  };

  return !managedPartner ? (
    <Box style={{ height: "calc(56px + 32px)" }}>
      <Loader height={56} style={{ marginTop: 16 }} />
    </Box>
  ) : (
    <>
      <InfosSelect
        {...rest}
        selectedItem={{
          ...managedPartner,
          title: translate("common.managing"),
          subTitle: dTranslate(managedPartner.title),
          avatar: {
            src: managedPartner.logo,
          },
        }}
        onItemClick={handleShow}
        data={managedPartners.map(partner => ({
          ...partner,
          title: translate("common.changeTo"),
          subTitle: dTranslate(partner.title),
          avatar: {
            src: partner.logo,
          },
        }))}
        isOpened={isOpened}
        onOpen={() => setIsOpened(state => !state)}
      />

      <SlapHole />
    </>
  );
};

SponsorInfosSelect.propTypes = cmsInfosSelectPropTypes;

export { SponsorInfosSelect };
