import React from "react";
import PropTypes from "prop-types";
import { MdFileDownload } from "react-icons/md";
import XLSX from "sheetjs-style";

//* CUSTOM IMPORTS
import { DialogModal } from "..";
import { IconedButton } from "../../atoms";
import { useTranslation } from "../../../hooks/translation";

const exportSheetsButtonPropTypes = {
  //* Add string inside button */
  placeholder: PropTypes.string.isRequired,

  //* Sheet Name, must be string */
  sheetsName: PropTypes.string.isRequired,

  //* Api call */
  callDownloadSheetsApi: PropTypes.func,
};

const exportSheetsButtonDefaultProps = {
  callDownloadSheetsApi: () => {},
};

function ExportSheetsButton({
  sheetsName,
  callDownloadSheetsApi,
  placeholder,
  ...rest
}) {
  //* STATES
  const [showModal, setShowModal] = React.useState(false);
  const [isDownloading, setIsDownloading] = React.useState(false);

  //* FUNCTIONS
  const { translate } = useTranslation();

  const dowloadUserExport = async () => {
    setIsDownloading(true);
    await callDownloadSheetsApi().then(response => {
      setShowModal(false);

      //* *Open the file to be downloaded */
      XLSX.writeFile(
        response.data,
        `${String(sheetsName)
          .toLowerCase()
          .normalize("NFD")
          .replace(/[\u0300-\u036f]/g, "")}.xlsx`
      );
    });
    setIsDownloading(false);
  };

  return (
    <>
      <IconedButton
        onClick={() => setShowModal(true)}
        icon={<MdFileDownload />}
        {...rest}
        placeholder={placeholder}
      />

      <DialogModal
        show={showModal}
        onConfirm={dowloadUserExport}
        onClose={() => setShowModal(false)}
        onCancel={() => setShowModal(false)}
        isLoading={isDownloading}
        description={
          isDownloading ? (
            <>
              Exportando o<strong>{sheetsName}...</strong>
            </>
          ) : (
            <>
              {translate("common.wantToExportThe")}
              <strong>{sheetsName}?</strong>
            </>
          )
        }
        title={translate("entities.reports.exportReport")}
      />
    </>
  );
}

ExportSheetsButton.propTypes = exportSheetsButtonPropTypes;

ExportSheetsButton.defaultProps = exportSheetsButtonDefaultProps;

export {
  ExportSheetsButton,
  exportSheetsButtonPropTypes,
  exportSheetsButtonDefaultProps,
};
