import React from "react";
import axios from "axios";
import { useSelector } from "react-redux";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { Dropdown } from "~/shared/components/organisms";
import Loader from "./Loader";

const IBGE_INITIAL_STATE_ID = 11;

function CitiesDropdown({ ...rest }) {
  const { stateId } = useSelector(state => state.registrationFields.form);
  const { attributes } = useSelector(s => s.user.profile.data);

  // STATES
  const currentStateId = stateId || IBGE_INITIAL_STATE_ID;
  const [options, setOptions] = React.useState([]);
  const [isFirstLoading, setIsFirstLaoding] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(true);

  // FUNCTIONS
  React.useEffect(() => {
    //  IBGE CITIES
    const listIBGECities = async () => {
      setIsLoading(true);

      await axios
        .get(
          `https://servicodados.ibge.gov.br/api/v1/localidades/estados/${currentStateId}/municipios`
        )
        .then(response => {
          const formattedOptions = response.data.map(c => {
            return {
              id: c.id,
              name: c.nome,
              value: c.id,
            };
          });

          const sortedOptions = formattedOptions.sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            }
            if (a.name > b.name) {
              return 1;
            }
            return 0;
          });

          setOptions(sortedOptions);
          setIsLoading(false);
          setIsFirstLaoding(false);
        })
        .catch(() => {
          setIsLoading(false);
          setIsFirstLaoding(false);
        });
    };

    listIBGECities();
  }, [currentStateId, setIsLoading, stateId]);

  return !isLoading ? (
    <Dropdown
      initialValue={
        isFirstLoading && isDataValid(attributes) ? attributes.city : null
      }
      options={options}
      withSearch
      {...rest}
    />
  ) : (
    <Loader width={328} />
  );
}

export default CitiesDropdown;
