import React from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";

// APIs
import { listDropdownAttributes } from "~/modules/signed/modules/cms/modules/admin/apis/cadasterItems/dropdownAttributes";

// CUSTOM IMPORTS
import { deleteItem, updateItem } from "~/shared/utils/lists";
import AttributeCell from "~/modules/signed/modules/cms/components/cells/DropdownAttributeCell";
import {
  IconedButton as AddButton,
  Typography,
  Trans,
} from "~/shared/components/atoms";
import { Container } from "./styles";
import { DraggableList } from "~/shared/components/molecules";
import { isStringEmpty } from "~/shared/utils/strings";
import Loader from "~/shared/components/molecules/cellLoaders/CardLoader";

import { useTranslation } from "~/shared/hooks/translation";

function DropdownAttributes({
  className,
  attributes,
  setAttributes,
  editMode,
  refCode,
}) {
  const fakeId = React.useRef(2);

  // STATES
  const [isLoading, setIsLoading] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const fetch = async () => {
      setIsLoading(true);

      await listDropdownAttributes({ attribute_ref: refCode }).then(
        response => {
          setAttributes(
            response.data.map(attribute => {
              return {
                ...attribute,
                id: String(attribute.id),
              };
            })
          );
          setIsLoading(false);
        }
      );
    };

    if (editMode) {
      fetch();
    } else {
      setAttributes([
        {
          id: String(1),
          title: `${translate("common.option")} ${1}`,
          isNewField: true,
        },
      ]);
    }
  }, [editMode, refCode, setAttributes, translate]);

  const onAddButton = async () => {
    setAttributes([
      ...attributes,
      {
        id: String(fakeId.current),
        title: `${translate("common.option")} ${fakeId.current}`,
        isNewField: true,
      },
    ]);
    fakeId.current += 1;
  };

  const onDeleteButton = async attributeId => {
    if (attributes.length === 1) {
      toast.error(translate("handlers.selectionField.atLeastOne"));
      return;
    }

    setAttributes(
      deleteItem({ itemId: String(attributeId), list: attributes })
    );
  };

  const onAttributeValueChange = attribute => {
    const { title } = attribute;

    if (!isStringEmpty(title))
      setAttributes(updateItem({ list: attributes, updatedItem: attribute }));
  };

  return (
    <Container className={className}>
      <Typography
        className="uppercase normal bold small"
        style={{ marginBottom: 10 }}
      >
        <Trans translationKey="common.fieldOptions">Opções do campo</Trans>
      </Typography>

      {isLoading ? (
        <div>
          <Loader height={48} width={328} />
          <Loader
            height={48}
            width={328}
            style={{ marginTop: 16, marginBottom: 16 }}
          />
          <Loader height={48} width={328} style={{ marginBottom: 16 }} />
        </div>
      ) : (
        <DraggableList
          data={attributes}
          onDragFinish={reorderedList => setAttributes(reorderedList)}
          renderCell={cell => (
            <AttributeCell
              attribute={cell}
              onDelete={onDeleteButton}
              onChange={onAttributeValueChange}
            />
          )}
        />
      )}

      <AddButton
        onClick={onAddButton}
        type="button"
        placeholder={translate("common.addOption")}
        style={{ margin: 0 }}
      />
    </Container>
  );
}

DropdownAttributes.propTypes = {
  className: PropTypes.string,
  attributes: PropTypes.arrayOf(PropTypes.object).isRequired,
  setAttributes: PropTypes.func.isRequired,
};
DropdownAttributes.defaultProps = {
  className: null,
};

export default DropdownAttributes;
