import api from "~/shared/services/api";

export const listBroadcastCategories = ({ page = 1, search = "" }) =>
  Promise.resolve(
    api.get("/category_broadcastroom", {
      params: {
        page,
        search,
      },
    })
  );

export const listBroadcastRooms = ({
  search = "",
  page = 1,
  onLive = false,
  categoryId,
}) =>
  Promise.resolve(
    api.get("/broadcastroom", {
      params: {
        search,
        page,
        onlive: onLive,
        category_id: categoryId,
      },
    })
  );
