import React from "react";
import { shade } from "polished";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Typography,
  avatarPropTypes,
  Box,
  Button,
  Icon,
  Line,
  stickyNotePropTypes,
  stickyNoteDefaultProps,
} from "../../../atoms";
import { Infos, simpleMenuPropTypes } from "../../../molecules";
import { useTranslation } from "../../../../hooks/translation";
import { Container, StickyNote, Buttons, SimpleMenu, Scroll } from "./styles";

const stickyNoteCellPropTypes = {
  /** Control all <b>Avatar</b> component props. See all props into
   * <b>Atoms/Avatar</b>
   */
  avatar: PropTypes.shape(avatarPropTypes),

  title: PropTypes.string.isRequired,

  subTitle: PropTypes.string,

  /** The content for StickyNote */
  content: PropTypes.string.isRequired,

  variant: PropTypes.oneOf(["post", "minified"]),

  color: stickyNotePropTypes.color,

  /** The counts for likes.
   * <i>Only works in <b>post</b> variant</i>
   */
  likesCount: PropTypes.number,

  /** The counts for comments.
   * <i>Only works in <b>post</b> variant</i>
   */
  commentsCount: PropTypes.number,

  /** Callback when user clicks into <b>LikeButton</b>.
   * <i>Only works in <b>post</b> variant</i>
   */
  onLike: PropTypes.func,

  /** Callback when user clicks into <b>Comment</b>.
   * <i>Only works in <b>post</b> variant</i>
   */
  onComment: PropTypes.func,

  /** Callback when user clicks into <b>Comment</b>.
   * <i>Only works in <b>post</b> variant</i>
   */
  onClick: PropTypes.func,

  /** If this props has data, add an menu at top right of this component.
   * See all props in <b>Molecules/SimpleMenu</b>
   */
  simpleMenu: PropTypes.shape(simpleMenuPropTypes),

  className: PropTypes.string,

  style: PropTypes.object,

  /** Control the posiiton for folde in <b>StickyNote</b> */
  foldPosition: stickyNotePropTypes.foldPosition,

  /** If <b>true</b> the <b>LikeButton</b> will change variant to "CONTAINED" */
  isLiked: PropTypes.bool,
};

const stickyNoteCellDefaultProps = {
  variant: "minified",
  avatar: null,
  subTitle: null,
  color: stickyNoteDefaultProps.color,
  likesCount: 0,
  commentsCount: 0,
  onLike: () => {},
  onComment: () => {},
  onClick: () => {},
  simpleMenu: null,
  className: "sticky-note",
  style: null,
  foldPosition: stickyNoteDefaultProps.foldPosition,
  isLiked: false,
};

const getMaxLines = variant => {
  if (variant === "post") return 10;

  return 3;
};

const hasCounters = (likes, comments) => {
  if (likes > 0 || comments > 0) return true;
};

/** This component is an <b>StickyNote</b> with some content inside. See all
 * <b>StickNote</b> props in <b>Atoms/StickNote</b>.
 */
const StickyNoteCell = ({
  variant,
  avatar,
  title,
  subTitle,
  content,
  likesCount,
  commentsCount,
  color,
  onLike,
  onComment,
  simpleMenu,
  className,
  isLiked,
  style,
  onClick,
  foldPosition,
  ...rest
}) => {
  const { translate } = useTranslation();

  return (
    <Container onClick={onClick} className={className} style={style}>
      <StickyNote
        {...rest}
        className="sticky-note"
        color={color}
        variant={variant}
        foldPosition={foldPosition}
      >
        <Infos
          avatar={{ ...avatar, size: 32 }}
          subTitle={subTitle}
          title={title}
        />
        <Scroll>
          <Typography
            maxLines={getMaxLines(variant)}
            style={{
              fontSize: variant === "post" ? 32 : 24,
              marginTop: variant === "post" ? 40 : 8,
            }}
          >
            {content}
          </Typography>
        </Scroll>

        {variant === "post" && (
          <>
            {hasCounters(likesCount, commentsCount) && (
              <>
                <Line color={shade(0.07, color)} style={{ marginTop: 20 }} />

                <Box display="flex" marginTop="8px" style={{ gap: 8 }}>
                  {likesCount > 0 && (
                    <Typography>
                      {likesCount}{" "}
                      {translate(
                        likesCount > 1 ? "common.likes" : "common.singleLike"
                      )}
                    </Typography>
                  )}

                  {commentsCount > 0 && (
                    <Typography>
                      {commentsCount}{" "}
                      {translate(
                        commentsCount > 1
                          ? "common.comments"
                          : "common.singleComment"
                      )}
                    </Typography>
                  )}
                </Box>
              </>
            )}
          </>
        )}
      </StickyNote>

      {variant === "post" && (
        <Buttons>
          <Button
            size="small"
            variant={isLiked ? "contained" : "surface"}
            icon={<Icon iconName="like" />}
            onClick={onLike}
          >
            {translate(isLiked ? "common.liked" : "common.like")}
          </Button>
          <Button
            size="small"
            variant="surface"
            icon={<Icon iconName="chat" />}
            onClick={onComment}
          >
            {translate("common.comment")}
          </Button>
        </Buttons>
      )}

      {simpleMenu && (
        <SimpleMenu
          {...simpleMenu}
          buttonIcon={<Icon iconName="menu" />}
          className="sticky-note-cell-menu"
        />
      )}
    </Container>
  );
};

StickyNoteCell.propTypes = stickyNoteCellPropTypes;

StickyNoteCell.defaultProps = stickyNoteCellDefaultProps;

export { StickyNoteCell, stickyNoteCellPropTypes, stickyNoteCellDefaultProps };
