import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";
import { ThemeContext } from "styled-components";

// ACTIONS
import {
  createCertificate,
  updateCertificate,
} from "~/modules/signed/modules/cms/modules/admin/store/certificates/actions";

//* CUSTOM IMPORTS
import { AdminForm as Form } from "~/modules/signed/modules/cms/components/AdminForm";
import { SwipeTabs } from "~/shared/components/molecules";
import { isDataValid, isListValid } from "~/shared/utils/validations";
import { openAdminCertificates } from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { Footer } from "./styles";

import InformationTab from "./components/InformationTab";
import ProgrammingTab from "./components/ProgrammingTab";
import AppearenceTab from "./components/AppearanceTab";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function CertificateForm({ certificate }) {
  const dispatch = useDispatch();
  const { toJsonTranslation, dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);
  const { isCreating, isUpdating } = useSelector(
    state => state.adminCertificates.list
  );

  //* STATES
  const textEditorInitialHtml = `<br/><br/><h1 class="ql-align-center"><br></h1><h1 class="ql-align-center"><br></h1><h1 class="ql-align-center"><strong class="ql-size-huge" style="color: ${
    colors.accent
  };">${translate(
    "entities.certificates.initialValue.title"
  )}</strong></h1><p class="ql-align-center"><strong class="ql-size-large" style="color: ${
    colors.accent
  };">
  ${translate(
    "entities.certificates.initialValue.subTitle"
  )}{{NREGISTRO}}</strong></p><p class="ql-align-center"><br></p><h2 class="ql-align-center"><br></h2><p class="ql-align-center"><span class="ql-size-huge">
  ${translate(
    "entities.certificates.initialValue.description.part1"
  )} </span><strong class="ql-size-huge">{{NOME}}</strong><span class="ql-size-huge">, ${translate(
    "entities.certificates.initialValue.description.part2"
  )} </span><strong class="ql-size-huge">{{NOME_EVENTO}}.</strong></p><p class="ql-align-center"><br></p><p class="ql-align-center"><br></p><p class="ql-align-center"><strong class="ql-size-huge">${translate(
    "entities.certificates.initialValue.footer"
  )} </strong><strong class="ql-size-huge" style="color: ${
    colors.accent
  };">{{WORKLOAD}}</strong></p><br/><br/><br/><br/><br/><br/>`;
  const editMode = isDataValid(certificate);
  const [formData, setFormData] = React.useState({});
  const [infoTabError, setInfoTabError] = React.useState(false);
  const [showModal, setShowModal] = React.useState(false);
  const [broadcastRoomIds, setBroadcastRoomIds] = React.useState([]);
  const [allBroadcastRooms, setAllBroadcastRooms] = React.useState(false);
  const [percentWatch, setPercentWatch] = React.useState(30);
  const [viewType, setViewType] = React.useState(
    editMode ? certificate.view_type : "public"
  );
  const [groupIds, setGroupIds] = React.useState([]);
  const [certificateBody, setCertificateBody] = React.useState("");
  const [background, setBackground] = React.useState(null);
  const [footer, setFooter] = React.useState(null);
  const [showSchedule, setShowSchedule] = React.useState(false);
  const [onlyLive, setOnlyLive] = React.useState(false);

  const formInitialData = {
    body_message: toJsonTranslation(textEditorInitialHtml),
  };

  //* FUNCTIONS

  React.useEffect(() => {
    if (editMode) {
      const { broadcastRooms, groups, show_schedule, only_live } = certificate;

      // Fill groups
      if (isListValid(groups)) setGroupIds(groups.map(group => group.id));

      // Fill broadcastRooms
      if (isListValid(broadcastRooms))
        setBroadcastRoomIds(
          broadcastRooms.map(broadcastRoom => broadcastRoom.id)
        );

      setAllBroadcastRooms(certificate.all_broadcast_rooms);
      setPercentWatch(certificate.min_watch_percent);
      setCertificateBody(certificate.body_message);
      setShowSchedule(show_schedule);
      setOnlyLive(only_live);
      setBackground(certificate.background);
      setFooter(certificate.footer);
    }
  }, [certificate, editMode]);

  async function handleSaveCertificate() {
    const certificateData = {
      ...formData,
      view_type: viewType,
      min_watch_percent: percentWatch,
      broadcastRoomIds,
      groupIds,
      allBroadcastRooms,
      background,
      footer,
      only_live: onlyLive,
      show_schedule: showSchedule,
    };

    if (!editMode) {
      dispatch(createCertificate({ certificateData }));
    } else {
      dispatch(
        updateCertificate({ certificateData, certificateId: certificate.id })
      );
    }
  }

  function onSubmitSuccess(data) {
    setFormData(data);
    setShowModal(true);
  }

  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH")
      openAdminCertificates();
  }, [isCreating, isUpdating]);

  const tabs = [
    { id: 1, name: translate("common.informations"), error: infoTabError },
    { id: 2, name: translate("common.schedule") },
    { id: 3, name: translate("common.appearance") },
  ];

  return (
    <>
      <Form
        yupValidation={{
          title: Yup.string().required("Título é obrigatório."),
          description: Yup.string().required("A Descrição é obrigatória."),
        }}
        onSubmit={data => onSubmitSuccess({ ...data, groupIds })}
        onError={() => setInfoTabError(true)}
        initialData={certificate || formInitialData}
      >
        <SwipeTabs
          tabs={tabs}
          size="small"
          alignment="left"
          childrenStyle={{ marginTop: 20 }}
        >
          <InformationTab
            groupIds={groupIds}
            setGroupIds={setGroupIds}
            viewType={viewType}
            setViewType={setViewType}
            banner={certificate && certificate.banner}
            con
          />

          <ProgrammingTab
            broadcastRooms={editMode && certificate.broadcastRooms}
            allBroadcastRooms={allBroadcastRooms}
            setAllBroadcastRooms={setAllBroadcastRooms}
            broadcastRoomIds={broadcastRoomIds}
            setBroadcastRoomIds={setBroadcastRoomIds}
            percentWatch={percentWatch}
            setPercentWatch={setPercentWatch}
            showSchedule={showSchedule}
            setShowSchedule={setShowSchedule}
            onlyLive={onlyLive}
            setOnlyLive={setOnlyLive}
          />

          <AppearenceTab
            showSchedule={showSchedule}
            certificateBody={certificateBody}
            onTextEditorChange={value => setCertificateBody(value)}
            onBackgroundChange={value => setBackground(value)}
            onFooterChange={value => setFooter(value)}
            background={background}
            footer={footer}
            broadcastRoomIds={broadcastRoomIds}
          />
        </SwipeTabs>

        <Footer
          entity={translate("entities.certificates.singular")}
          entityName={formData && dTranslate(formData.title)}
          onCancel={openAdminCertificates}
          isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
          modalType={editMode ? "update" : "create"}
          showModal={showModal}
          onConfirm={handleSaveCertificate}
        />
      </Form>
    </>
  );
}

CertificateForm.propTypes = {
  certificate: PropTypes.object.isRequired,
};

export default CertificateForm;
