import React from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  listBroadcastRoomSchedule,
  clearSchedules,
  deleteSchedule,
} from "~/modules/signed/modules/cms/modules/admin/store/schedules/actions";
import { isDataValid, isListValid } from "~/shared/utils/validations";
import { ScheduleCell } from "~/shared/components/organisms/cells";
import { getFullHours } from "~/shared/utils/dates";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function BroadcastRoomScheduleList({
  broadcastRoomId,
  onCellClick,
  activedCell,
}) {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: schedules } = useSelector(state => state.adminSchedule.list);

  //* FUNCTIONS
  const { dTranslate, dTranslateSpeakers } = useDynamicTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(broadcastRoomId))
      dispatch(listBroadcastRoomSchedule(broadcastRoomId));

    return () => {
      dispatch(clearSchedules());
      isMounted = false;
    };
  }, [broadcastRoomId, dispatch]);

  const handleDelete = scheduleId => {
    dispatch(deleteSchedule(scheduleId));
  };

  return (
    isListValid(schedules) && (
      <div>
        {schedules.map(schedule => {
          const { id, title, speakers, start_time, end_time } = schedule;

          return (
            <ScheduleCell
              id={id}
              variant={schedule.id === activedCell && "accent"}
              key={schedule.id}
              schedule={schedule}
              speakers={dTranslateSpeakers(speakers)}
              title={dTranslate(title)}
              entityName={dTranslate(title)}
              startTime={getFullHours(start_time)}
              endTime={getFullHours(end_time)}
              onClick={() => onCellClick(schedule)}
              onEdit={() => onCellClick(schedule)}
              style={{ marginBottom: 10 }}
              onDelete={() => handleDelete(id)}
            />
          );
        })}
      </div>
    )
  );
}

BroadcastRoomScheduleList.propTypes = {
  broadcastRoomId: PropTypes.number,
  onCellClick: PropTypes.func,
  activedCell: PropTypes.number,
};

BroadcastRoomScheduleList.defaultProps = {
  broadcastRoomId: null,
  onCellClick: null,
  activedCell: null,
};

export default BroadcastRoomScheduleList;
