import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import { listMarketRoomsSuccessful } from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

export function* list() {
  try {
    const response = yield call(api.get, "/admin/marketroom");

    if (validResponseStatus(response.status)) {
      const marketRooms = response.data;

      yield put(listMarketRoomsSuccessful(marketRooms));
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([takeLatest(types.LIST.REQUEST, list)]);
