import React from "react";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import { Input, Typography, Box } from "~/shared/components/atoms";
import { Form, DaysWeekPicker } from "~/shared/components/molecules";
import { Dropdown } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { getFullDate, getFullHours, minutesToTime } from "~/shared/utils/dates";
import { getKeySelectValue, TIME_OPTIONS } from "~/shared/utils/selects";
import { getInputValue, isDateInputToday } from "~/shared/validators/form";
import { useSponsorSchedule } from "../../hooks";
import { TimeContainer } from "./styles";

function SponsorScheduleForm({ ...rest }) {
  const { translate } = useTranslation();
  const {
    meetingConfig,
    setScheduleFormData,
    saveMeetingConfig,
  } = useSponsorSchedule();

  //* FUNCTIONS
  const handleInputChange = (key, e) =>
    setScheduleFormData(key, e.target.value);

  const handleDropdownChange = (key, value) => setScheduleFormData(key, value);

  const today = React.useMemo(() => getFullDate(new Date()), []);

  /** This function will set the end min value, based on startTime input value. */
  const getEndTimeMin = () => {
    const startTimeValue = getInputValue("startTime");

    if (!startTimeValue) return null;

    const meetingTime = meetingConfig.meeting_time || 0;
    const breakTime = meetingConfig.break_time || 0;

    const [inputHours, inputMinutes] = startTimeValue.split(":");

    /** The min value for this input will be the value of startTime more meetingTime and breakTime
     * values.
     */
    const result =
      Number(inputHours) * 60 + Number(inputMinutes) + meetingTime + breakTime;

    const { hours, minutes } = minutesToTime(result);

    const formmatedHours = hours < 10 ? `0${hours}` : hours;
    const formmatedMinutes = minutes < 10 ? `0${minutes}` : minutes;

    return `${formmatedHours}:${formmatedMinutes}`;
  };

  const handleOnSubmit = () => {
    saveMeetingConfig();
  };

  return (
    <Form
      {...rest}
      initialData={meetingConfig}
      onSubmit={handleOnSubmit}
      onError={error => console.log(error)}
      yupValidation={{
        meeting_time: Yup.string().required(
          translate("components.select.isRequired")
        ),
        break_time: Yup.string().required(
          translate("components.select.isRequired")
        ),
        startDate: Yup.string().required(
          translate("components.input.startDate.isRequired")
        ),
        startTime: Yup.string().required(
          translate("components.input.startTime.isRequired")
        ),
        endDate: Yup.string().required(
          translate("components.input.endDate.isRequired")
        ),
        endTime: Yup.string().required(
          translate("components.input.endTime.isRequired")
        ),
      }}
    >
      <Box>
        <Typography
          className="subTitle bold accent"
          style={{ marginBottom: 10 }}
        >
          {translate("entities.meetings.information")}
        </Typography>

        <Dropdown
          icon="time"
          name="meeting_time"
          title={translate("components.select.meetingTime.title")}
          placeholder={translate("components.select.meetingTime.placeholder")}
          style={{ marginBottom: 16, position: "relative", zIndex: 2 }}
          options={TIME_OPTIONS}
          initialValue={getKeySelectValue(
            "value",
            meetingConfig.meeting_time,
            TIME_OPTIONS,
            "name"
          )}
          onChange={value => handleDropdownChange("meeting_time", value)}
        />

        <Dropdown
          icon="time"
          name="break_time"
          title={translate("components.select.meetingBreakTime.title")}
          placeholder={translate(
            "components.select.meetingBreakTime.placeholder"
          )}
          initialValue={getKeySelectValue(
            "value",
            meetingConfig.break_time,
            TIME_OPTIONS,
            "name"
          )}
          options={TIME_OPTIONS}
          onChange={value => handleDropdownChange("break_time", value)}
        />
      </Box>

      <Box marginTop="24px">
        <Typography
          className="subTitle bold accent"
          style={{ marginBottom: 10 }}
        >
          {translate("common.availability")}
        </Typography>

        <TimeContainer>
          <Input
            id="startDate"
            name="startDate"
            type="date"
            title={translate("components.input.startDate.title")}
            onChange={e => handleInputChange("startDate", e)}
            min={today}
            max={getInputValue("endDate")}
          />

          <Input
            id="endDate"
            name="endDate"
            type="date"
            title={translate("components.input.endDate.title")}
            onChange={e => handleInputChange("endDate", e)}
            min={getInputValue("startDate") || today}
          />
        </TimeContainer>

        <TimeContainer>
          <Input
            id="startTime"
            name="startTime"
            type="time"
            title={translate("components.input.startTime.title")}
            onChange={e => handleInputChange("startTime", e)}
            min={isDateInputToday("startDate") && getFullHours(new Date())}
          />

          <Input
            id="endTime"
            name="endTime"
            type="time"
            title={translate("components.input.endTime.title")}
            onChange={e => handleInputChange("endTime", e)}
            min={getEndTimeMin()}
          />
        </TimeContainer>

        <Typography
          className="normal small bold uppercase"
          style={{ marginBottom: 10 }}
        >
          {translate("common.daysOfWeek")}
        </Typography>

        <DaysWeekPicker
          selectedDays={meetingConfig.daysWeek}
          onChange={days => setScheduleFormData("daysWeek", days)}
        />
      </Box>
    </Form>
  );
}

export { SponsorScheduleForm };
