import React from "react";

//* CUSTOM IMPORTS
import { useImportUsers, TABS_INDEX } from "../../hooks";
import { useCMS } from "../../../../../../../../hooks";

import GroupList from "../../../../../../../../components/lists/GroupList";
import { Box, Typography, Checkbox } from "~/shared/components/atoms";
import { RadioGroup, Form } from "~/shared/components/molecules";
import { Dropdown } from "~/shared/components/organisms";
import { ImportTemplate } from "~/shared/components/templates";
import {
  openAdminUserImportMatch,
  openAdminUserImportReview,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";

const RADIO_ID = {
  GENERATE_RANDOM_PASSWORD: 1,
  SELECT_FIELD_TO_BE_PASSWORD: 2,
};

const ImportUserSettings = () => {
  const {
    tabIndex,
    dataToBeImported,
    sheetConfig,
    setGroups,
    setSendEmail,
    setPasswordField,
    cadasterItems,
  } = useImportUsers();

  const { setFooterButtons } = useCMS();
  const { translate } = useTranslation();

  // * STATES
  const [checkedRadio, setCheckedRadio] = React.useState(
    RADIO_ID.GENERATE_RANDOM_PASSWORD
  );

  //* FUNCTIONS
  React.useEffect(() => {
    if (tabIndex === TABS_INDEX.SETTINGS)
      setFooterButtons({
        isVisible: true,
        saveButton: translate("common.advance"),
        cancelButton: translate("common.return"),
        onClick: openAdminUserImportReview,
        onCancel: openAdminUserImportMatch,
      });
  }, [setFooterButtons, tabIndex, translate]);

  const handleRadioClick = radio => {
    setCheckedRadio(radio.id);

    if (radio.id === RADIO_ID.GENERATE_RANDOM_PASSWORD) setPasswordField(null);

    if (radio.id === RADIO_ID.SELECT_FIELD_TO_BE_PASSWORD)
      setPasswordField(cadasterItems[0]);
  };

  return (
    <ImportTemplate
      title={translate("entities.users.newUserSettings")}
      subTitle={
        <>
          <strong>{dataToBeImported()}</strong>{" "}
          {translate(
            dataToBeImported() > 1
              ? "entities.users.usersWillBeImported"
              : "entities.users.usersWillBeImported"
          )}
          .
        </>
      }
    >
      <Box display="flex">
        <Box flex={1}>
          <Typography
            className="uppercase tiny opaque bold"
            style={{ marginBottom: 8 }}
          >
            {translate("entities.users.assignUsersToGroups")}
          </Typography>

          <GroupList
            selectedGroups={sheetConfig.groups.map(group => group.id)}
            onSelect={(_, groups) => setGroups(groups)}
            style={{ flex: 1 }}
          />
        </Box>

        <Box flex={1} marginLeft="24px">
          <Typography
            className="uppercase tiny opaque bold"
            style={{ marginBottom: 8 }}
          >
            {translate("common.loginSettings")}
          </Typography>

          <RadioGroup
            orientation="column"
            name="loginSettings"
            gap={24}
            data={[
              {
                id: RADIO_ID.GENERATE_RANDOM_PASSWORD,
                label: translate("pages.userImportSettings.radioGroup.radio1"),
              },
              {
                id: RADIO_ID.SELECT_FIELD_TO_BE_PASSWORD,
                label: translate("pages.userImportSettings.radioGroup.radio2"),
              },
            ]}
            checked={checkedRadio}
            onRadioClick={handleRadioClick}
          />

          {checkedRadio === RADIO_ID.SELECT_FIELD_TO_BE_PASSWORD && (
            <Form style={{ marginTop: 8 }}>
              <Dropdown
                name="passwordField"
                initialValue={
                  cadasterItems.length > 0 && cadasterItems[0].title
                }
                options={cadasterItems.map(item => ({
                  id: item.id,
                  name: item.title,
                  value: item.ref_code,
                  ...item,
                }))}
                onChange={(_, cadasterItem) => setPasswordField(cadasterItem)}
              />

              <Checkbox
                style={{ marginTop: 16, position: "relative", right: 9 }}
                checked={sheetConfig && sheetConfig.sendEmail}
                onClick={() => setSendEmail(!sheetConfig.sendEmail)}
                placeholder={translate(
                  "common.sendAnEmailWithInstructionsForFirstAccess"
                )}
              />
            </Form>
          )}
        </Box>
      </Box>
    </ImportTemplate>
  );
};

export default ImportUserSettings;
