import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography, tagPropTypes } from "../../atoms";
import { CellList } from "../../molecules";
import Loader from "../../molecules/cellLoaders/CardLoader";
import { Header, Card, Tag } from "./styles";

const CellLoader = ({ lastIndex }) => (
  <Loader
    height={30}
    width={70}
    style={{ height: 30, width: 70, marginBottom: !lastIndex && 16 }}
  />
);

CellLoader.propTypes = {
  /** If <b>True</b> disable the marginBottom of this component. */
  lastIndex: PropTypes.bool.isRequired,
};

const tagSchedulePropTypes = {
  /** The title will be rendered at top of this component. */
  title: PropTypes.string,
  /** An Array of Arrays, the first array will render the columns,
   * the inner array will render the lines of each colum
   */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number.isRequired,
      title: PropTypes.string,
      data: PropTypes.arrayOf(PropTypes.shape(tagPropTypes)).isRequired,
    })
  ).isRequired,
  /** Callback when user click in some <b>Tag</b>, return the tag value.
   *
   * Example: (tag) => {}
   */
  onClick: PropTypes.func,
  /** If <b>true</b> render <b>Loaders</b> instead of the default component. */
  isLoading: PropTypes.bool,
  /** An component to be rendered when this component has no data. */
  emptyComponent: PropTypes.element,

  withScroll: PropTypes.bool,

  scrollStyle: PropTypes.object,
};

const tagScheduleDefaultProps = {
  onClick: () => {},
  isLoading: false,
  title: null,
  emptyComponent: null,
  withScroll: false,
  scrollStyle: null,
};

const TagSchedule = ({
  title,
  data,
  onClick,
  isLoading,
  emptyComponent,
  withScroll,
  scrollStyle,
  ...rest
}) => {
  return (
    <Card {...rest}>
      <Header>
        <Typography className="subTitle centered bold white">
          {title}
        </Typography>
      </Header>

      <CellList
        disableWrap
        isLoaded={!isLoading}
        data={data}
        gap={32}
        withScroll={withScroll}
        scrollStyle={scrollStyle}
        listStyle={{ justifyContent: "center", padding: "0 16px 16px" }}
        emptyComponent={emptyComponent}
        renderCellLoader={() => (
          <>
            <CellLoader />
            <CellLoader />
            <CellLoader />
            <CellLoader lastIndex />
          </>
        )}
        renderCell={shift => (
          <CellList
            headerComponent={
              shift.title && (
                <Typography
                  className="subTitle normal centered bold"
                  style={{ marginBottom: 16 }}
                >
                  {shift.title}
                </Typography>
              )
            }
            orientation="column"
            isLoaded
            gap={16}
            data={shift.data}
            renderCell={tag => {
              const { disabled, isBlocked, isSelected } = tag;

              return (
                <Tag
                  disabled={disabled}
                  onClick={() => onClick(tag)}
                  hoverable
                  isSelected={isSelected}
                  size="large"
                  variant={isBlocked ? "opaque" : "outlined"}
                  borderRadius={4}
                  key={tag.id}
                >
                  {tag.name}
                </Tag>
              );
            }}
          />
        )}
      />
    </Card>
  );
};

TagSchedule.propTypes = tagSchedulePropTypes;

TagSchedule.defaultProps = tagScheduleDefaultProps;

export { TagSchedule, tagSchedulePropTypes, tagScheduleDefaultProps };
