import produce from "immer";
import types from "./types";
import { isDataValid, isListValid } from "~/shared/utils/validations";
import { deleteItem } from "~/shared/utils/lists";

const INITIAL_STATE = {
  categories: {
    data: [],
    isLoaded: false,
  },

  object: {
    data: {},
    isLoaded: false,
    partnerIds: [],
    tagIds: [],
    groupIds: [],
    scheduleIds: [],
  },
};

export default function broadcastRoomsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // CATEGORIES
      case types.CATEGORIES.RECEIVE: {
        const { broadcastRoomCategories } = action.payload;

        if (isDataValid(broadcastRoomCategories))
          draft.categories.data = broadcastRoomCategories;

        draft.categories.isLoaded = true;

        break;
      }

      // REORDER CATEGORIES
      case types.CATEGORIES.REORDER: {
        const { reorderedList } = action.payload;

        if (isDataValid(reorderedList)) draft.categories.data = reorderedList;

        break;
      }

      // ADD CATEGORIES
      case types.CATEGORIES.ADD: {
        const { newCategory } = action.payload;

        if (isDataValid(newCategory)) {
          const { order } = newCategory;
          const auxArray = draft.categories.data;

          const newCategoryData = {
            ...newCategory,
            broadcastRooms: [],
          };

          if (order) {
            auxArray.splice(order - 1, 0, newCategoryData);
          } else {
            auxArray.push(newCategoryData);
          }

          draft.categories.data = auxArray;
        }

        break;
      }
      case types.CATEGORIES.CHANGE: {
        const { updatedCategory } = action.payload;

        if (isDataValid(updatedCategory)) {
          const filteredArray = draft.categories.data.filter(
            obj => obj.id !== updatedCategory.id
          );

          const hasUpdatedArray = draft.categories.data.find(
            obj => obj.id === updatedCategory.id
          );

          const { broadcastRooms } = hasUpdatedArray;

          const updatedCategoryData = {
            ...updatedCategory,
            broadcastRooms,
          };

          filteredArray.splice(
            updatedCategoryData.order - 1,
            0,
            updatedCategoryData
          );

          draft.categories.data = filteredArray;
        }
        break;
      }

      case types.CATEGORIES.REMOVE: {
        const { categoryId } = action.payload;

        let auxArray = draft.categories.data;

        auxArray = deleteItem({ itemId: categoryId, list: auxArray });

        draft.categories.data = auxArray;

        break;
      }

      // REMOVE
      case types.BROADCAST_ROOMS.REMOVE: {
        const { categoryId, broadcastRoomId } = action.payload;

        const categories = draft.categories.data;

        const categoryIndex = categories.findIndex(c => c.id === categoryId);

        const category = categories.find(c => c.id === categoryId);

        const { broadcastRooms } = category;

        const filteredArray = broadcastRooms.data.filter(
          b => b.id !== broadcastRoomId
        );

        categories[categoryIndex].broadcastRooms.data = filteredArray;

        draft.categories.data = categories;

        break;
      }

      case types.BROADCAST_ROOMS.PAGINATE: {
        const { categoryId, broadcastRooms } = action.payload;

        const categories = draft.categories.data;

        const categoryIndex = categories.findIndex(c => c.id === categoryId);

        const category = categories.find(c => c.id === categoryId);

        const newArray = {
          ...broadcastRooms,
          data: [...category.broadcastRooms.data, ...broadcastRooms.data],
        };

        categories[categoryIndex].broadcastRooms = newArray;

        draft.categories.data = categories;

        break;
      }

      // SHOW
      case types.show.SUCCESSFUL: {
        const { broadcastRoom } = action.payload;

        if (isDataValid(broadcastRoom)) {
          draft.object.data = broadcastRoom;

          const { tags, partners } = broadcastRoom.room;

          // Fill tagIds
          if (isListValid(tags)) {
            const tagIds = tags.map(tag => tag.id);

            draft.object.tagIds = tagIds;
          }

          // Fill groupIds
          if (isListValid(broadcastRoom.groups)) {
            const groupIds = broadcastRoom.groups.map(group => group.id);

            draft.object.groupIds = groupIds;
          }

          // Fill partnerIds
          if (isListValid(partners)) {
            const partnerIds = partners.map(partner => partner.id);

            draft.object.partnerIds = partnerIds;
          }
        }

        draft.object.isLoaded = true;

        break;
      }

      // FORM
      case types.form.SET_TAGS_IDS: {
        const { tagIds } = action.payload;

        draft.object.tagIds = tagIds;

        break;
      }
      case types.form.SET_GROUP_IDS: {
        const { groupIds } = action.payload;

        draft.object.groupIds = groupIds;

        break;
      }
      case types.form.SET_PARTNER_IDS: {
        const { partnerIds } = action.payload;

        draft.object.partnerIds = partnerIds;

        break;
      }
      case types.form.SET_SCHEDULE_IDS: {
        const { scheduleIds } = action.payload;

        draft.object.scheduleIds = scheduleIds;

        break;
      }
      case types.form.ADD_SCHEDULE_ID: {
        const { scheduleId } = action.payload;

        if (isDataValid(scheduleId))
          draft.object.scheduleIds = [...draft.object.scheduleIds, scheduleId];
        break;
      }
      case types.form.REMOVE_SCHEDULE_ID: {
        const { scheduleId } = action.payload;

        if (isDataValid(scheduleId)) {
          const filteredArray = draft.object.scheduleIds.filter(
            obj => obj !== scheduleId
          );

          draft.object.scheduleIds = filteredArray;
        }

        break;
      }

      // CLEAR FORM
      case types.form.CLEAR: {
        draft.object.data = {};
        draft.object.partnerIds = [];
        draft.object.tagIds = [];
        draft.object.groupIds = [];
        draft.object.scheduleIds = [];

        draft.object.isLoaded = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.object.partnerIds = [];
        draft.object.tagIds = [];
        draft.object.groupIds = [];
        draft.object.data = {};
        draft.object.isLoaded = false;

        break;
      }
      default:
    }
  });
}
