import React from "react";
import PropTypes from "prop-types";
import { MdFileDownload } from "react-icons/md";
import { ThemeContext } from "styled-components";

//* APIs
import {
  generateCertificate,
  getCertificateSchedule,
} from "~/modules/signed/modules/client/apis/certificates";

//* CUSTOM IMPORTS
import { numberToTime } from "~/shared/utils/dates";

import {
  Typography,
  TextArea,
  Tag,
  Box,
  Button,
  Trans,
} from "~/shared/components/atoms";
import { Modal, Table } from "~/shared/components/molecules";

const propTypes = {
  certificate: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
};

function CertificateModal({ certificate, ...rest }) {
  const { colors } = React.useContext(ThemeContext);

  //* STATES
  const [isGenerating, setIsGenerating] = React.useState(false);
  const [downloadUrl, setDownloadUrl] = React.useState(null);
  const [schedule, setSchedule] = React.useState([]);
  const [isScheduleLoaded, setIsScheduleLoaded] = React.useState(false);

  //* FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      if (certificate)
        await getCertificateSchedule({
          certificateId: certificate.id,
        }).then(response => {
          setSchedule(response.data);
          setIsScheduleLoaded(true);
        });
    };

    fetch();

    return () => {
      setSchedule([]);
      setIsScheduleLoaded(false);
    };
  }, [certificate]);

  const handleButtonClick = async () => {
    //* *If user already generate the certificate do nothing */
    if (downloadUrl) return;

    setIsGenerating(true);

    await generateCertificate({
      certificateId: certificate.id,
    }).then(response => setDownloadUrl(response.data.download_url));

    setIsGenerating(false);
  };

  if (!certificate) return null;

  const {
    title,
    description,
    available,
    total_time_needed: totalTimeNeeded,
    watched_percent: watchedPercent,
    needed_percent: neededPercent,
  } = certificate;

  return (
    <Modal
      {...rest}
      title={title}
      withScroll
      titleColor={colors.normal}
      style={{ maxWidth: 520, width: "100%" }}
    >
      <Tag color={available ? colors.greenCorrect : colors.gray}>
        {available ? (
          <Trans translationKey="common.availableForDownload">
            Disponível para download
          </Trans>
        ) : (
          <Trans translationKey="common.unavailableForDownload">
            Indisponível para download
          </Trans>
        )}
      </Tag>

      <Typography className="opaque bold" style={{ marginTop: 10 }}>
        <Trans translationKey="common.description">Descrição</Trans>
      </Typography>

      <TextArea defaultValue={description} />

      <Box display="flex" marginTop="10px" marginBottom="20px" width="100%">
        <Box flex={1}>
          <Typography className="opaque bold">
            {" "}
            <Trans translationKey="common.workload">Carga Horária</Trans>
          </Typography>
          <Typography className="bold">
            {numberToTime(totalTimeNeeded)}
          </Typography>
        </Box>

        <Box flex={1}>
          <Typography className="opaque bold">
            <Trans translationKey="common.assistedPercentage">
              % assistida necessária
            </Trans>
          </Typography>
          <Typography className="bold">{neededPercent}%</Typography>
          <Typography className="opaque bold">
            <Trans translationKey="common.youWatched">Você assistiu</Trans>{" "}
            {String(watchedPercent).substr(0, 4) || 0}%
          </Typography>
        </Box>
      </Box>

      <Table
        isLoading={!isScheduleLoaded}
        titles={["Conteúdo", "Carga Horária"]}
        rows={schedule.map(s => ({
          ...s,
          total_time: numberToTime(s.total_time),
        }))}
        style={{ marginBottom: 20 }}
        empty={{
          message: (
            <Trans translationKey="entities.schedule.empty.message">
              Sem programações
            </Trans>
          ),
          description: (
            <Trans translationKey="entities.certificates.noSchedule">
              Este certificado aínda não possuí uma programação
            </Trans>
          ),
        }}
      />

      {isGenerating && (
        <Typography
          className="opaque bold"
          style={{ width: "80%", margin: "0 auto", marginBottom: 8 }}
        >
          <Trans translationKey="entities.certificates.generating">
            Gerando Certificado...
          </Trans>
        </Typography>
      )}

      {available && (
        <Button
          href={downloadUrl}
          target="_blank"
          download={title}
          loading={isGenerating}
          onClick={handleButtonClick}
          icon={<MdFileDownload />}
          style={{ width: "80%", margin: "0 auto" }}
        >
          {downloadUrl ? (
            <Trans translationKey="entities.certificates.download">
              Baixar Certificado
            </Trans>
          ) : (
            <Trans translationKey="entities.certificates.generate">
              Gerar certificado
            </Trans>
          )}
        </Button>
      )}
    </Modal>
  );
}

CertificateModal.propTypes = propTypes;

export default CertificateModal;
