import styled, { keyframes } from "styled-components";

//* CUSTOM IMPORTS
import { AvatarFrame as AvatarFrameC } from "~/shared/components/molecules";
import { layers, medias } from "~/shared/styles";

const animation = keyframes`
  from{
    right: -100%;
  }

  to{
    right: 0%;
  }
`;

export const Container = styled.div`
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: ${layers.openedChats};
  margin-right: 24px;

  @media (max-width: ${medias.mobile}) {
    margin-right: 0px;
  }
`;

export const AvatarFrame = styled(AvatarFrameC)`
  cursor: pointer;
  align-self: flex-end;
  border-radius: 8px 8px 0 0;
  position: relative;
  z-index: ${props => layers.openedChats - props.index};
  animation: ${animation} 0.5s ease;

  @media (max-width: ${medias.mobile}) {
    margin-right: 24px;
  }
`;
