import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";

//* PROVIDERS
import { SponsorCMSProvider } from "../modules/sponsor/hooks";
import { AdminCMSProvider } from "../modules/admin/hooks";

//* CUSTOM IMPORTS
import { useSigned } from "~/modules/signed/hooks";
import HashListener from "./components/HashListener";
import { USER_TYPES } from "~/shared/utils/user";
import { CMSTemplate } from "~/shared/components/templates";
import { ProviderSideMenu } from "./components/ProviderSideMenu";

const CMSContext = createContext(null);

function CMSProvider({ children, title }) {
  const location = useLocation();
  const { user } = useSigned();

  //* STATES

  const [selectedCrumb, setSelectedCrumb] = React.useState(null);
  const [titleState, setTitle] = React.useState(title);
  const [editMode, setEditMode] = React.useState(false);
  const [breadCrumbs, setBreadCrumbs] = React.useState([]);
  const [footerButtons, setFooterButtons] = React.useState(null);
  const [onBottomReached, setOnBottomReached] = React.useState(() => {});

  //* REF
  const scrollRef = React.useRef(null);

  //* FUNCTIONS
  React.useMemo(() => {
    setTitle(title);
  }, [title]);

  React.useEffect(() => {
    if (breadCrumbs.length) {
      const { pathname } = location;

      const divisions =
        //* *Remove all number from the pathname and his "/" caracter, then count all "/"
        //* * caracter except the first one*/
        String(pathname)
          .replace(/\/\d+/g, "")
          .split("/").length - 2;

      setSelectedCrumb(divisions - 1);
    }
  }, [breadCrumbs.length, location]);

  function scrollToTop() {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = 0;
    }
  }

  function scrollTop(px) {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = px;
    }
  }

  const handleBreadcrumbOnChange = (crumb, index) => {
    setSelectedCrumb(index);
  };

  /** You can implement the footerButton with all his props, see more
   * in Molecules/FooterButtons */
  const showFooterButtons = React.useCallback(
    footerButtonsData =>
      setFooterButtons({ ...footerButtonsData, isVisible: true }),
    []
  );

  const closeFooterButtons = React.useCallback(
    () => setFooterButtons(null),
    []
  );

  /** This function will define the Provider that the logged user will receive
   * based on its access_type.
   */
  const Provider = React.useMemo(() => {
    const { ADMIN, SPONSOR, EDITOR } = USER_TYPES;

    switch (user.access_type) {
      case ADMIN.value:
      case EDITOR.value:
        return AdminCMSProvider;

      case SPONSOR.value:
        return SponsorCMSProvider;

      default:
        break;
    }
  }, [user.access_type]);

  return (
    <CMSContext.Provider
      value={{
        //* States
        breadCrumbs,

        //* Functions
        scrollTop,
        setEditMode,
        scrollToTop,
        setBreadCrumbs,
        setFooterButtons,
        showFooterButtons,
        setOnBottomReached,
        closeFooterButtons,
      }}
    >
      <Provider>
        <CMSTemplate
          title={titleState}
          sideMenuComponent={<ProviderSideMenu />}
          scroll={{
            editMode,
            onBottomReached,
            ref: ref => {
              if (!scrollRef.current) scrollRef.current = ref;
            },
          }}
          breadCrumbs={{
            data: breadCrumbs,
            onChange: handleBreadcrumbOnChange,
            selectedCrumb,
          }}
          footerButtons={footerButtons}
        >
          {children}
        </CMSTemplate>
      </Provider>

      <HashListener />
    </CMSContext.Provider>
  );
}

CMSProvider.propTypes = {
  children: PropTypes.any.isRequired,
  title: PropTypes.string,
};

CMSProvider.defaultProps = {
  title: "",
};

function useCMS() {
  const context = useContext(CMSContext);

  if (!context) throw new Error("useCMS must be used within CMSProvider");

  return context;
}

export { CMSProvider, useCMS };
