import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import { getFullDate, getFullHours, createDate } from "~/shared/utils/dates";
/* import { isDataValid } from "~/shared/utils/ValidationUtils";
import { getYupErrors } from "~/shared/utils/FormUtils"; */
import { Modal, Form } from "~/shared/components/molecules";
import { Button } from "~/shared/components/atoms";
import { Input, Dropdown } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import i18n from "~/shared/translation/i18n";

const repeatTypes = [
  {
    id: 1,
    name: i18n.t("common.minutes"),
  },
  {
    id: 2,
    name: i18n.t("common.hours"),
  },
  {
    id: 3,
    name: i18n.t("common.days"),
  },
];

function PersonalizeRepeatModal({
  show,
  notification,
  onClose,
  setStartDate,
  setEndDate,
  setRepeat,
}) {
  //* FUNCTIONS
  const { translate } = useTranslation();

  const formRef = React.useRef(null);
  const setInputDateMinValue = () => getFullDate(new Date());
  const [formInitialData, setFormInitialData] = React.useState({});
  const [dropDownValue, setDropDownValue] = React.useState(
    translate("common.minutes")
  );

  const convertRepeatType = React.useCallback(
    repeat => {
      if (notification) {
        if (repeat < 60) {
          setDropDownValue(translate("common.minutes"));
        }
        if (repeat > 60 && repeat < 1440) {
          repeat /= 60;
          setDropDownValue(translate("common.hours"));

          return repeat;
        }
        if (repeat > 1440) {
          repeat /= 1440;
          setDropDownValue(translate("common.days"));
          return repeat;
        }
      }
      return repeat;
    },
    [notification, translate]
  );

  React.useEffect(() => {
    if (notification) {
      const { repeat, end_date, start_date } = notification;

      setFormInitialData({
        repeatNumber: convertRepeatType(repeat),
        endDay: getFullDate(end_date),
        endTime: end_date && getFullHours(end_date),
        startDay: getFullDate(start_date),
        startTime: start_date && getFullHours(start_date),
      });
    }
  }, [convertRepeatType, notification]);

  const handleSubmit = repeat => {
    const startDate = createDate({
      date: repeat.startDay,
      time: repeat.startTime,
    });

    const endDate = createDate({
      date: repeat.endDay,
      time: repeat.endTime,
    });

    setRepeat(repeat.repeatNumber);

    setStartDate(startDate);

    setEndDate(endDate);

    if (repeat.repeatType === "Horas") {
      const repeatNumberHours = repeat.repeatNumber * 60;
      setRepeat(repeatNumberHours);
    }
    if (repeat.repeatType === "Dias") {
      const repeatNumberDays = repeat.repeatNumber * 1440;
      setRepeat(repeatNumberDays);
    }

    onClose();
  };

  const handleDateInputChange = (e, inputId) => {
    const { value } = e.target;
    const timeInput = document.getElementById(inputId);

    const [year, mounth, day] = String(value).split("-");
    const today = new Date();
    const todayYear = today.getFullYear();
    const todayMounth = today.getUTCMonth() + 1;
    const todayDay = today.getUTCDate();

    let formmatedDay = todayDay;
    let formattedMounth = todayMounth;

    if (todayDay < 10) formmatedDay = `0${todayDay}`;

    if (todayMounth < 10) formattedMounth = `0${todayMounth}`;

    //* If data is today, add a min value into time input
    if (
      year === String(todayYear) &&
      mounth === String(formattedMounth) &&
      day === String(formmatedDay)
    ) {
      timeInput.setAttribute("min", getFullHours(new Date()));
    } else {
      timeInput.setAttribute("min", null);
    }

    return null;
  };

  return (
    <Modal
      title={translate("common.repeatPersonalize")}
      show={show}
      onClose={onClose}
    >
      <Form
        onSubmit={handleSubmit}
        ref={formRef}
        initialData={formInitialData}
        yupValidation={{
          repeatNumber: Yup.string().required(
            translate("handlers.field.isRequired")
          ),
          repeatType: Yup.string().required(
            translate("handlers.field.isRequired")
          ),
          startDay: Yup.date().typeError(
            translate("handlers.startDate.isRequired")
          ),
          startTime: Yup.string().required(
            translate("handlers.startTime.isRequired")
          ),
          endDay: Yup.date()
            .typeError(translate("handlers.endDate.isRequired"))
            .min(
              Yup.ref("startDay"),
              translate("handlers.endDate.isNotEqualsOrGreaterThanStartDate")
            ),
          endTime: Yup.string().required(
            translate("handlers.endTime.isRequired")
          ),
        }}
      >
        <fieldset style={{ display: "flex" }}>
          <Input
            title="Repetir a cada"
            name="repeatNumber"
            type="number"
            min={1}
            style={{ minWidth: 0, maxWidth: 215 }}
          />
          <Dropdown
            name="repeatType"
            noRegistration
            initialValue={dropDownValue}
            options={repeatTypes}
          />
        </fieldset>

        <fieldset style={{ display: "flex", marginTop: 40 }}>
          <Input
            title={translate("components.input.startDate.title")}
            name="startDay"
            type="date"
            onChange={e => handleDateInputChange(e, "startTime")}
            min={setInputDateMinValue()}
          />
          <Input
            title={translate("components.input.startTime.title")}
            id="startTime"
            name="startTime"
            type="time"
            style={{ marginLeft: 20 }}
          />
        </fieldset>

        <fieldset style={{ display: "flex", marginTop: 40 }}>
          <Input
            title={translate("components.input.endDate.title")}
            name="endDay"
            type="date"
            onChange={e => handleDateInputChange(e, "endTime")}
            min={setInputDateMinValue()}
          />
          <Input
            title={translate("components.input.endTime.title")}
            name="endTime"
            id="endTime"
            type="time"
            min="startTime"
            style={{ marginLeft: 20 }}
          />
        </fieldset>

        <Button loading={false} type="submit" style={{ marginTop: 30 }}>
          {translate("common.save")}
        </Button>
      </Form>
    </Modal>
  );
}

PersonalizeRepeatModal.propTypes = {
  show: PropTypes.bool,
  onClose: PropTypes.func,
  setStartDate: PropTypes.func.isRequired,
  setEndDate: PropTypes.func.isRequired,
  setRepeat: PropTypes.func.isRequired,
  notification: PropTypes.object.isRequired,
};

PersonalizeRepeatModal.defaultProps = {
  show: false,
  onClose: () => {},
};

export default PersonalizeRepeatModal;
