import React from "react";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Button, Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useUserChat } from "../../../../chat";

const ClientChatButton = () => {
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();
  const {
    openChatList,
    closeChatList,
    isChatListOpen,
    unreadMessages,
  } = useUserChat();

  return (
    <Button
      isSelected={isChatListOpen}
      onClick={isChatListOpen ? closeChatList : openChatList}
      variant="primary"
      icon={
        <Icon
          iconName="chat"
          variant="white"
          hasNew={unreadMessages > 0}
          badgeOutlineColor={colors.primary}
        />
      }
      style={{ marginLeft: 8 }}
      tip={translate("common.messages")}
    />
  );
};

export { ClientChatButton };
