import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";

//* CUSTOM IMPORTS
import {
  getConfig,
  updateConfig,
  getRanking,
} from "~/modules/signed/modules/cms/modules/admin/apis/gamification";
import { useTranslation } from "~/shared/hooks/translation";
import { API_STATUS } from "~/shared/utils/apis";
import { useGamification } from "~/modules/signed/hooks/gamification";

const AdminGamificationContext = createContext(null);

const AdminGamificationProvider = ({ children }) => {
  //* STATES
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [settings, setSettings] = React.useState({
    isEnabled: true,
    showUserRanking: true,
  });
  const [scoreSettings, setScoreSettings] = React.useState(null);
  const [rankingSettings, setRankingSettings] = React.useState(null);
  const [scoreApiStatus, setScoreApiStatus] = React.useState(API_STATUS.IDLE);
  const [rankingApiStatus, setRankingApiStatus] = React.useState(
    API_STATUS.IDLE
  );
  const [ranking, setRanking] = React.useState({
    data: [],
    isLoaded: false,
    page: 1,
    lastPage: 1,
  });

  //* FUNCTIONS
  const { translate } = useTranslation();
  const {
    updateGamificationSettings,
    enableGamification,
    enableShowUserRanking,
  } = useGamification();

  React.useEffect(() => {
    getConfig().then(response => {
      const {
        is_enabled: isEnabled,
        show_user_ranking: showUserRanking,
        users_on_gold,
        users_on_silver,
        users_on_bronze,
        gold_frame,
        silver_frame,
        bronze_frame,
        first_access_points,
        broadcast_check_in_points,
        rate_broadcast_points,
        rate_schedule_points,
        send_question_points,
        first_chat_room_points,
        like_message_points,
        reply_message_points,
        first_reaction_points,
        friend_connection_points,
        first_chat_points,
        partner_check_in_points,
        partner_button_points,
        partner_social_points,
        bookmark_partner_points,
        bookmark_broadcast_points,
        bookmark_schedule_points,
        bookmark_speaker_points,
        broadcast_button_points,
      } = response.data;

      setSettings({ isEnabled, showUserRanking });
      setRankingSettings({
        users_on_gold,
        users_on_silver,
        users_on_bronze,
        gold_frame,
        silver_frame,
        bronze_frame,
      });
      setScoreSettings({
        first_access_points,
        broadcast_check_in_points,
        rate_broadcast_points,
        rate_schedule_points,
        send_question_points,
        first_chat_room_points,
        like_message_points,
        reply_message_points,
        first_reaction_points,
        friend_connection_points,
        first_chat_points,
        partner_check_in_points,
        partner_button_points,
        partner_social_points,
        bookmark_partner_points,
        bookmark_broadcast_points,
        bookmark_schedule_points,
        bookmark_speaker_points,
        broadcast_button_points,
      });

      setIsLoaded(true);
    });
  }, []);

  /** This function will enable or disable gamification, based on his last state */
  const handleEnableGamification = () => {
    const isEnabled = !settings.isEnabled;

    updateConfig({
      is_enabled: isEnabled,
    }).then(() => {
      setSettings(state => ({ ...state, isEnabled }));
      enableGamification(isEnabled);

      if (!isEnabled) {
        updateConfig({
          show_user_ranking: false,
        }).then(() => {
          setSettings(state => ({ ...state, showUserRanking: false }));
        });
      }

      toast.success(
        translate(
          isEnabled
            ? "entities.gamification.enableMessage"
            : "entities.gamification.disableMessage"
        )
      );
    });
  };

  /** This function will enable or disable show user ranking, based on his last state */
  const handleShowUserRanking = () => {
    const isShowable = !settings.showUserRanking;

    updateConfig({
      show_user_ranking: isShowable,
    }).then(() => {
      setSettings(state => ({ ...state, showUserRanking: isShowable }));
      enableShowUserRanking(isShowable);

      toast.success(
        translate(
          isShowable
            ? "entities.gamification.showUserRankingMessage"
            : "entities.gamification.hideUserRankingMessage"
        )
      );
    });
  };

  /** This function will send to Server the gamification points settings */
  const saveScoreSettings = pointsData => {
    setScoreApiStatus(API_STATUS.PENDING);

    updateConfig(pointsData).then(() => {
      setScoreApiStatus(API_STATUS.SUCCESS);
      toast.success(
        translate("entities.gamification.scoreSettings.saveMessage")
      );
    });
  };

  /** This function will send to Server the gamification ranking settings */
  const saveRankingSettings = rankingData => {
    setRankingApiStatus(API_STATUS.PENDING);

    updateConfig(rankingData).then(response => {
      /** SideEffect to update all gamification settings for cms and client */
      updateGamificationSettings(response.data);
      toast.success(
        translate("entities.gamification.rankingSettings.saveMessage")
      );
      setRankingApiStatus(API_STATUS.SUCCESS);
    });
  };

  const listRanking = React.useCallback((page, search) => {
    setRankingApiStatus(API_STATUS.PAGING);

    getRanking(page, search).then(response => {
      setRanking({ ...response.data, isLoaded: true });
      setRankingApiStatus(API_STATUS.SUCCESS);
    });
  }, []);

  /** This function will update the user score inside ranking. */
  const updateUserRankingScore = React.useCallback((score, userId) => {
    setRanking(state => {
      const findedUserIndex = state.data.findIndex(
        user => user.user_id === userId
      );

      if (findedUserIndex !== -1) {
        state.data[findedUserIndex] = {
          ...state.data[findedUserIndex],
          points: score,
        };
      }

      return state;
    });
  }, []);

  return (
    <AdminGamificationContext.Provider
      value={{
        //* States
        settings,
        isLoaded,
        ranking,
        scoreSettings,
        scoreApiStatus,
        rankingSettings,
        rankingApiStatus,

        //* Functions
        listRanking,
        updateUserRankingScore,
        saveRankingSettings,
        saveScoreSettings,
        handleEnableGamification,
        handleShowUserRanking,
      }}
    >
      {children}
    </AdminGamificationContext.Provider>
  );
};

AdminGamificationProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

function useAdminGamification() {
  const context = useContext(AdminGamificationContext);

  if (!context)
    throw new Error(
      "useAdminGamification must be used within AdminGamificationContext"
    );

  return context;
}

export { AdminGamificationProvider, useAdminGamification };
