/* eslint-disable react-hooks/exhaustive-deps */
import React, { createContext } from "react";
import { useLocation } from "react-router-dom";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";

// ACTIONS
import { signOut } from "~/modules/auth/store/auth/actions";

// NAVIGATIONS
import { openSignIn } from "~/shared/routes/navigation";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { useSocket } from "..";
import { useAlert } from "~/shared/hooks/alert";
import { useTranslation } from "~/shared/hooks/translation";

const AcessLogContext = createContext(null);

const AccessLogProvider = ({ children }) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const { subscribe, closeChannel } = useSocket();
  const { translate } = useTranslation();

  //* REFs
  const accessLogRef = React.useRef(null);

  //* STATES
  const page = location.pathname + location.hash;

  const [previousHistory, setPreviousHistory] = React.useState({
    prevPage: null,
    prevCode: null,
  });

  //* FUNCTIONS
  const { showAlert } = useAlert();

  const getCode = () => String(page).replace(/\D+/g, "");

  const getAccessLogData = () => {
    //* *REGEX to remove code from pathname */
    const formattedPage = String(page).replace(/\/\d+/g, "");
    const formattedPrevPage = String(previousHistory.prevPage).replace(
      /\/\d+/g,
      ""
    );

    return {
      from_page: formattedPrevPage, // Last page path
      from_code: previousHistory.prevCode, // Last code page
      to_page: formattedPage, // Current page path
      to_code: getCode(), // Current code page
    };
  };

  React.useEffect(() => {
    accessLogRef.current = subscribe(`accesslog:all`);

    if (isDataValid(accessLogRef.current)) {
      accessLogRef.current.emit("accessLog", getAccessLogData());

      accessLogRef.current.on("log_out_user", data => {
        const { logout_type: logoutType } = data;

        let alertData = {};

        switch (logoutType) {
          case "access_revoked":
            alertData = {
              title: translate("common.duplicateAccess"),
              description: translate("common.duplicateAccessMessage"),
              onlyConfirm: true,
              confirmButton: translate("pages.signIn.submitButton"),
              onConfirm: openSignIn,
            };

            dispatch(signOut());
            break;

          default:
            break;
        }

        showAlert(alertData);
      });
    }
  }, [subscribe]);

  React.useMemo(() => {
    if (accessLogRef.current)
      if (accessLogRef.current.state !== "closed")
        accessLogRef.current.emit("accessLog", getAccessLogData());

    setPreviousHistory({
      prevCode: getCode(),
      prevPage: page,
    });
  }, [page]);

  React.useEffect(() => {
    return () => {
      closeChannel(accessLogRef.current);
    };
  }, [closeChannel]);

  return (
    <AcessLogContext.Provider value={{}}>{children}</AcessLogContext.Provider>
  );
};

AccessLogProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useAccessLog() {
  const context = React.useContext(AcessLogContext);

  if (!context)
    throw new Error(
      "useAccessLog must be used within AccessLogProvider and SocketProvider"
    );

  return context;
}

export { AccessLogProvider, useAccessLog };
