import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { getFullDate, isToday, getFullHours } from "~/shared/utils/dates";
import { Checkbox } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { REPEAT_TYPE } from "~/shared/utils/types";

import { Container, Input, Dropdown, TimesContainer } from "./styles";

const repeatTypes = [
  {
    id: 1,
    name: REPEAT_TYPE.NOREPEAT.name,
    value: REPEAT_TYPE.NOREPEAT.value,
  },
  {
    id: 2,
    name: REPEAT_TYPE.REPEAT15.name,
    value: REPEAT_TYPE.REPEAT15.value,
  },
  {
    id: 3,
    name: REPEAT_TYPE.REPEAT30.name,
    value: REPEAT_TYPE.REPEAT30.value,
  },
  {
    id: 4,
    name: REPEAT_TYPE.REPEAT1.name,
    value: REPEAT_TYPE.REPEAT1.value,
  },
  {
    id: 5,
    name: REPEAT_TYPE.PERSONALIZE.name,
    value: REPEAT_TYPE.PERSONALIZE.value,
  },
];

function NotificationsRoomInformation({
  notification,
  sendNow,
  setSendNow,
  setRepeat,
}) {
  // STATES
  const { translate } = useTranslation();

  React.useEffect(() => {
    setRepeat(notification ? notification.repeat : REPEAT_TYPE.NOREPEAT.value);

    return () => {
      setRepeat([]);
    };
  }, [notification, setRepeat]);

  function handleCheckboxClick() {
    // setIsChecked(!isChecked);
    setSendNow(!sendNow);
  }

  function handleOnChange(value) {
    setRepeat(value);
  }

  function convertRepeatType() {
    if (notification) {
      if (notification.repeat === 15) return REPEAT_TYPE.REPEAT15.name;
      if (notification.repeat === 30) return REPEAT_TYPE.REPEAT30.name;
      if (notification.repeat === 60) return REPEAT_TYPE.REPEAT1.name;
      if (notification.start_date) return REPEAT_TYPE.PERSONALIZE.name;
    } else {
      return "Não repetir";
    }

    return "Não repetir";
  }

  const handleDateInputChange = e => {
    const { value } = e.target;

    if (value)
      if (isToday({ date: value })) {
        const timeInput = document.getElementById("scheduleTime");

        /** Add an min into time input if the date is today */
        if (timeInput) timeInput.setAttribute("min", getFullHours(new Date()));
      }
  };

  return (
    <>
      <Container id="notifcations-room-infos">
        <Input
          name="title"
          title={translate("components.input.notificationTitle.title")}
          placeholder={translate(
            "components.input.notificationTitle.placeholder"
          )}
        />
        <Input
          className="input-description"
          name="description"
          isTextArea
          title={translate("components.input.notificationDescription.title")}
          placeholder={translate(
            "components.input.notificationDescription.placeholder"
          )}
          type="textarea"
        />
        <Input
          name="link"
          title={translate("components.input.notificationLink.title")}
          placeholder={translate(
            "components.input.notificationLink.placeholder"
          )}
        />
        <TimesContainer sendNow={sendNow}>
          <Input
            className="time-input"
            title={translate("components.input.shippingDate.title")}
            id="scheduleDate"
            name="schedule_date"
            type="date"
            disabled={sendNow}
            min={getFullDate(new Date())}
            onChange={e => handleDateInputChange(e)}
          />

          <Input
            id="scheduleTime"
            className="time-input"
            title={translate("components.input.schedule.title")}
            name="time"
            type="time"
            disabled={sendNow}
          />

          <div id="checkbox-area">
            <Checkbox
              checked={sendNow}
              onClick={handleCheckboxClick}
              name="send_now"
              initialValue={sendNow}
              placeholder={
                notification && notification.send_now
                  ? translate("common.sent")
                  : translate("common.sendImmediately")
              }
            />
          </div>
        </TimesContainer>
        <Dropdown
          name="repeat"
          initialValue={convertRepeatType}
          noRegistration
          options={repeatTypes}
          onChange={handleOnChange}
          title={translate("common.repeat")}
        />
      </Container>
    </>
  );
}

NotificationsRoomInformation.propTypes = {
  notification: PropTypes.object.isRequired,
  repeat: PropTypes.object.isRequired,
  setRepeat: PropTypes.func.isRequired,
  sendNow: PropTypes.bool.isRequired,
  setSendNow: PropTypes.func.isRequired,
};

export default NotificationsRoomInformation;
