import styled, { css } from "styled-components";

export const Container = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
`;

export const ArrowButton = styled.button`
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 6px ${props => props.theme.colors.shadow};
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  font-weight: bold;
  width: 24px;

  svg {
    height: 16px;
    width: 16px;
    color: #000;
  }

  transition: background 0.3s;
  border-radius: 8px;

  &:hover,
  &:active {
    background: ${props => props.theme.colors.accent};
    color: #fff;

    svg {
      color: #fff;
    }
  }

  ${props =>
    props.disabled &&
    css`
      opacity: 0.35;
      pointer-events: none;
    `}
`;
export const PageNumbers = styled.div`
  display: flex;
  margin: 0 8px;
`;

export const PageButton = styled(ArrowButton)`
  background: none;
  border: none;
  box-shadow: none;

  ${props =>
    props.isActive &&
    css`
      background: ${props.theme.colors.accent};
      color: #fff;
    `}

    ${props =>
      props.invisible &&
      css`
        display: none;
      `}

  ${props =>
    props.isDisabled &&
    css`
      pointer-events: none;
    `}
`;
