/* eslint-disable react/prop-types */
import React from "react";

//* CUSTOM IMPORTS
import {
  Touchable,
  ContentWrapper,
  CRUDOptionsMenu,
  Tags,
  CustomButtonContent,
} from "./styles";
import { Avatar, Typography, Box } from "../../../../../atoms";

/** SilverPartnerCell is an variant of PartnerCell, to see all props see the
 * PartnerCell
 */
const SilverPartnerCell = ({
  title,
  subTitle,
  avatar,
  tags,
  crudOptions,
  customButton,
  onClick,
  ...rest
}) => (
  <Touchable {...rest}>
    <ContentWrapper onClick={onClick}>
      <Avatar src={avatar} size={56} style={{ margin: "0 auto" }} />

      <Box marginTop="8px">
        <Typography maxLines={1} className="subTitle normal bold centered">
          {title}
        </Typography>

        {subTitle && (
          <Typography maxLines={1} className="opaque bold small centered">
            {subTitle}
          </Typography>
        )}
      </Box>

      <Tags tags={tags} trim />
    </ContentWrapper>

    {crudOptions && (
      <CRUDOptionsMenu
        {...crudOptions}
        className="silver-sponsor-admin-options"
        entityName={title}
      />
    )}

    {customButton && (
      <CustomButtonContent className="silver-sponsor-custom-button">
        {customButton}
      </CustomButtonContent>
    )}
  </Touchable>
);

export { SilverPartnerCell };
