import React from "react";
import PropTypes from "prop-types";

//* APIs
import {
  getRanking,
  showUserPoints,
  getGamificationSettings,
} from "~/modules/signed/modules/client/apis/gamification";
import { API_STATUS } from "~/shared/utils/apis";
import { useSigned } from "~/modules/signed/hooks";

const GamificationContext = React.createContext(null);

const INITIAL_DATA = {
  data: [],
  isLoaded: false,
  page: 1,
  lastPage: 1,
  isPaging: false,
};

const GamificationProvider = ({ children }) => {
  //* STATES
  const [userPoints, setUserPoints] = React.useState(null);
  const [rankingList, setRankingList] = React.useState(INITIAL_DATA);
  const [rankingApiStatus, setRankingApiStatus] = React.useState(
    API_STATUS.IDLE
  );
  const [podium, setPodium] = React.useState([]);
  const [settings, setSettings] = React.useState(null);
  const [isSettingsLoaded, setIsSettingsLoaded] = React.useState(false);

  //* FUNCTIONS
  const { userId: myUserId } = useSigned();

  React.useEffect(() => {
    setIsSettingsLoaded(false);
    getGamificationSettings().then(response => {
      setSettings(response.data);
      setIsSettingsLoaded(true);
    });
  }, []);

  React.useEffect(() => {
    if (settings) {
      if (settings.is_enabled)
        showUserPoints({ userId: myUserId }).then(response =>
          setUserPoints(response.data)
        );
    }
  }, [myUserId, settings]);

  const listRanking = React.useCallback((page, search) => {
    setRankingApiStatus(API_STATUS.PAGING);

    getRanking({ page, search }).then(response => {
      setRankingList({ ...response.data, isLoaded: true });

      /** Store the first 3 positions in podium */
      if (page === 1 && !search)
        setPodium(response.data.data.filter(rank => rank.rank_position <= 3));

      setRankingApiStatus(API_STATUS.SUCCESS);
    });
  }, []);

  const updateGamificationSettings = React.useCallback(newSettings => {
    setSettings(newSettings);
  }, []);

  /** This function wil return an avatar frame based on user position in gamification */
  const renderGamificationFrame = React.useCallback(
    position => {
      if (settings && position) {
        const {
          users_on_gold: usersOnGold,
          users_on_silver: usersOnSilver,
          users_on_bronze: usersOnBronze,
          gold_frame: goldFrame,
          silver_frame: silverFrame,
          bronze_frame: bronzeFrame,
        } = settings;

        const goldPosition = Number(usersOnGold);
        const silverPosition = Number(goldPosition) + Number(usersOnSilver);
        const bronzePosition = Number(silverPosition) + Number(usersOnBronze);

        if (position <= goldPosition) return goldFrame;

        if (position > goldPosition && position <= silverPosition)
          return silverFrame;

        if (position > silverPosition && position <= bronzePosition)
          return bronzeFrame;

        return null;
      }
    },
    [settings]
  );

  const handleUserPoints = () => {
    if (settings) if (settings.is_enabled) return userPoints;

    return null;
  };

  const enableGamification = React.useCallback(isEnabled => {
    setSettings(state => ({
      ...state,
      is_enabled: isEnabled,
    }));
  }, []);

  const enableShowUserRanking = React.useCallback(showUserRanking => {
    setSettings(state => ({
      ...state,
      show_user_ranking: showUserRanking,
    }));
  }, []);

  const updateMyRanking = React.useCallback(userPointsData => {
    if (userPointsData)
      setUserPoints(state => ({ ...state, ...userPointsData }));
  }, []);

  return (
    <GamificationContext.Provider
      value={{
        //* States
        userPoints: handleUserPoints(),
        podium,
        rankingApiStatus,
        rankingList,
        settings,
        isSettingsLoaded,

        //* Functions
        listRanking,
        updateMyRanking,
        renderGamificationFrame,
        updateGamificationSettings,
        enableGamification,
        enableShowUserRanking,
      }}
    >
      {children}
    </GamificationContext.Provider>
  );
};

GamificationProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useGamification = () => {
  const context = React.useContext(GamificationContext);

  if (!context)
    throw new Error("useGamification must be used within GamificationProvider");

  return context;
};

export { GamificationProvider, useGamification };
