import React from "react";
import PropTypes from "prop-types";
import { MdMoreVert } from "react-icons/md";
import { useSelector, useDispatch } from "react-redux";
import { ThemeContext } from "styled-components";

// ACTIONS
import { deleteUser } from "~/modules/signed/modules/cms/modules/admin/store/users/actions";

// CUSTOM IMPORTS
import {
  ContainerWrapper,
  Container,
  StrongText,
  Text,
  AdminOptions,
} from "./styles";
import { openEditUser, openUserDetails } from "~/shared/routes/navigation";
import { DialogModal } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function UserCell({ user }) {
  const { colors } = React.useContext(ThemeContext);
  const dispatch = useDispatch();
  const { isDeleting } = useSelector(state => state.adminUsers.list);

  // STATES
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  function handleCellClick() {
    openUserDetails(user.id, user.name);
  }

  const fillItems = () => {
    const items = [
      {
        id: 1,
        name: translate("entities.users.edit"),
        function: () => openEditUser({ userId: user.id }),
      },
      {
        id: 2,
        name: translate("entities.users.delete"),
        function: () => setShowDeleteModal(true),
        color: colors.redWrong,
      },
    ];

    return items;
  };

  React.useMemo(() => {
    if (isDeleting === "FINISH") setShowDeleteModal(false);
  }, [isDeleting]);

  return (
    <>
      <ContainerWrapper>
        <Container id="user-cell-content" onClick={handleCellClick}>
          <StrongText>{user.name}</StrongText>
          <Text>{user.email}</Text>
        </Container>

        <AdminOptions
          className="user-cell-options"
          fillItems={fillItems}
          buttonIcon={<MdMoreVert />}
        />
      </ContainerWrapper>

      {showDeleteModal && (
        <DialogModal
          show={showDeleteModal}
          title={translate("entities.users.delete")}
          description={`${translate("entities.users.deleteMessage")} ${
            user.name
          }?`}
          onClose={() => setShowDeleteModal(false)}
          onCancel={() => setShowDeleteModal(false)}
          onConfirm={() => dispatch(deleteUser(user.id))}
          isLoading={isDeleting}
        />
      )}
    </>
  );
}

UserCell.propTypes = {
  user: PropTypes.object.isRequired,
};

export default UserCell;
