import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { DashboardReportCard, Empty } from "~/shared/components/molecules";
import { HoverWrapper, Icon } from "~/shared/components/atoms";
import { openMaterialDownloaded } from "~/shared/routes/navigation";
import { useDownloadMaterial } from "../../../../hooks";
import { isDataValid } from "~/shared/utils/validations";
import { Container } from "./styles";

import { useTranslation } from "~/shared/hooks/translation";

function PerformanceTab({ material }) {
  const { getTotalCount, materialTotalCount, isLoaded } = useDownloadMaterial();

  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(material)) {
      const { id } = material;

      getTotalCount({ materialId: id });
    }

    return () => {
      isMounted = false;
    };
  }, [getTotalCount, material]);

  const isSent = materialTotalCount > 0;

  return (
    <Container>
      {isSent ? (
        <>
          <HoverWrapper>
            <DashboardReportCard
              dotColor="#2BFD6A"
              reportItems={[
                {
                  id: 1,
                  counter: materialTotalCount,
                  description: translate(
                    "entities.downloadMaterials.performanceTab.peopleDownloaded"
                  ),
                },
              ]}
              onClick={() => openMaterialDownloaded(material.id)}
              isLoaded={isLoaded}
              style={{ marginLeft: 0 }}
            />
          </HoverWrapper>
        </>
      ) : (
        <Empty
          style={{ margin: "0 24px" }}
          icon={<Icon iconName="download" />}
          message={translate(
            "entities.downloadMaterials.performanceTab.empty.message"
          )}
          description={translate(
            "entities.downloadMaterials.performanceTab.empty.description"
          )}
        />
      )}
    </Container>
  );
}

PerformanceTab.propTypes = {
  material: PropTypes.object.isRequired,
};

export default PerformanceTab;
