import React from "react";
import PropTypes from "prop-types";
import { useSelector, useDispatch } from "react-redux";

//* ACTIONS
import { sendBroadcastRoomReaction } from "~/modules/signed/modules/client/pages/BroadcastRoom/store/reactions/actions";

//* CUSTOM IMPORTS
import { Reactions } from "~/shared/components/organisms";

function ReactionSocket({ className, broadcastRoomId }) {
  const dispatch = useDispatch();
  const { lastReaction } = useSelector(
    state => state.wsReactions.broadcastRoom
  );

  //* FUNCTIONS
  const sendReaction = emojiType =>
    dispatch(sendBroadcastRoomReaction({ emojiType, broadcastRoomId }));

  return (
    <Reactions
      onReactionClick={sendReaction}
      className={className}
      lastReaction={lastReaction}
    />
  );
}

ReactionSocket.propTypes = {
  className: PropTypes.string,
  broadcastRoomId: PropTypes.number.isRequired,
};

ReactionSocket.defaultProps = {
  className: null,
};

export default ReactionSocket;
