import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container } from "./styles";
import { Typography } from "~/shared/components/atoms";
import { SwipeTabs } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useGamification } from "~/modules/signed/hooks/gamification";

// TAB FRAGMENTS
import { GeneralTab } from "./components/GeneralTab";
import { ConnectionsTab } from "./components/ConnectionsTab";
import { BookmarkTab } from "./components/BookmarkTab";
import { GamificationTab } from "./components/GamificationTab";

const TABS = {
  GENERAL: 0,
  CONNECTIONS: 1,
  BOOKMARKS: 2,
  GAMIFICATION: 3,
};

const ProfileBody = ({ user, itsMe }) => {
  // STATES
  const { settings, isSettingsLoaded } = useGamification();
  const { translate } = useTranslation();
  const [tabIndex, setTabIndex] = React.useState(TABS.GENERAL);

  // TABS
  const tabs = [
    {
      id: 1,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.general.singular")}
        </Typography>
      ),
    },

    {
      id: 2,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.connection.plural")}
        </Typography>
      ),
    },

    {
      id: 3,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.bookmark.plural")}
        </Typography>
      ),
    },
  ];

  if (itsMe && isSettingsLoaded)
    if (settings.is_enabled)
      tabs.push({
        id: 4,
        name: (
          <Typography className="bold uppercase">
            {translate("entities.gamification.singular")}
          </Typography>
        ),
      });

  const handleOnTabChange = index => {
    setTabIndex(index);
  };

  return (
    <Container id="profile-tabs">
      <SwipeTabs
        variant="scrollable"
        controlled
        index={tabIndex}
        onChange={handleOnTabChange}
        size="small"
        alignment="left"
        tabs={tabs}
      >
        <GeneralTab
          user={user}
          itsMe={itsMe}
          callIf={tabIndex === TABS.GENERAL}
        />

        <ConnectionsTab
          user={user}
          itsMe={itsMe}
          callIf={tabIndex === TABS.CONNECTIONS}
        />

        <BookmarkTab callIf={tabIndex === TABS.BOOKMARKS} />

        {itsMe && <GamificationTab callIf={tabIndex === TABS.GAMIFICATION} />}
      </SwipeTabs>
    </Container>
  );
};

ProfileBody.propTypes = {
  user: PropTypes.object.isRequired,
  itsMe: PropTypes.bool.isRequired,
};

ProfileBody.defaultProps = {};

export { ProfileBody };
