import React from "react";
import PropTypes from "prop-types";
import { useField } from "@unform/core";
import ReactInputMask from "react-input-mask";
import TextareaAutosize from "react-autosize-textarea";

//* CUSTOM IMPORTS
import { isStringEmpty } from "../../../../../../../../shared/utils/strings";
import { INPUT_TYPES, setInputMask, setInputMode } from "~/shared/utils/inputs";
import {
  Icon,
  InputContainer,
} from "../../../../../../../../shared/components/atoms";

const dynamicInputPropTypes = {
  /** Change the input render based on inputTYpe */
  inputType: PropTypes.oneOf([
    INPUT_TYPES.PARAGRAPH.value,
    INPUT_TYPES.TEXT.value,
    INPUT_TYPES.PHONE.value,
    INPUT_TYPES.TEXT.value,
    INPUT_TYPES.TIME.value,
    INPUT_TYPES.EMAIL.value,
    INPUT_TYPES.ADRESS.value,
    INPUT_TYPES.PASSWORD.value,
  ]),

  /** Name is required to reference the input inside the form  */
  name: PropTypes.string.isRequired,

  /** Add an <b>*</b> in the input title */
  isRequired: PropTypes.bool,

  /** Add an title above input */
  title: PropTypes.string,

  /** Add an icon in the left */
  icon: PropTypes.string,

  /** Force an type for the input, accept all the default type of an <b>HTMLInput</b> */
  type: PropTypes.string,

  className: PropTypes.string,

  style: PropTypes.object,
};

const dynamicInputDefaultProps = {
  title: "",
  icon: null,
  type: null,
  isRequired: false,
  inputType: INPUT_TYPES.TEXT.value,
  className: null,
  style: null,
};

const DynamicInput = ({
  name,
  title,
  icon,
  type,
  inputType,
  isRequired,
  className,
  style,
  ...rest
}) => {
  const { fieldName, defaultValue, error, registerField } = useField(name);

  //* REFS
  const inputRef = React.useRef(null);

  //* STATES
  const isDefaultInput =
    inputType === INPUT_TYPES.TEXT.value ||
    inputType === INPUT_TYPES.TIME.value ||
    inputType === INPUT_TYPES.EMAIL.value ||
    inputType === INPUT_TYPES.ADRESS.value ||
    inputType === INPUT_TYPES.PASSWORD.value;
  const isParagraph = inputType === INPUT_TYPES.PARAGRAPH.value;

  const [isFocused, setIsFocused] = React.useState(false);
  const [isFilled, setIsFilled] = React.useState(defaultValue || false);

  //* FUNCTIONS
  React.useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, registerField]);

  //* FUNCTIONS
  function setInputType() {
    switch (inputType) {
      case INPUT_TYPES.TIME.value:
        return "time";

      default:
        return "text";
    }
  }

  function renderInput() {
    if (isDefaultInput) {
      return (
        <input
          name={name}
          ref={inputRef}
          defaultValue={defaultValue}
          type={type || setInputType()}
          {...rest}
        />
      );
    }

    if (isParagraph)
      return (
        <TextareaAutosize
          {...rest}
          defaultValue={defaultValue}
          ref={inputRef}
        />
      );

    return (
      <ReactInputMask
        {...rest}
        inputMode={setInputMode(inputType)}
        mask={setInputMask(inputType)}
        defaultValue={defaultValue}
        type={type || setInputType()}
        inputRef={ref => {
          inputRef.current = ref;
        }}
      />
    );
  }

  const renderInputContainerVariant = () => {
    if (isFocused) return "focus";

    if (error) return "error";

    if (isFilled) return "filled";

    return null;
  };

  return (
    <InputContainer
      title={title}
      isRequired={isRequired}
      className={className}
      style={style}
      errorMessage={error}
      variant={renderInputContainerVariant()}
      onFocus={() => setIsFocused(true)}
      onBlur={() => {
        setIsFocused(false);

        if (inputRef.current)
          setIsFilled(!isStringEmpty(inputRef.current.value));
      }}
      icon={<Icon iconName={icon} />}
      {...rest}
    >
      {renderInput()}
    </InputContainer>
  );
};

DynamicInput.propTypes = dynamicInputPropTypes;
DynamicInput.defaultProps = dynamicInputDefaultProps;

export { DynamicInput, dynamicInputPropTypes, dynamicInputDefaultProps };
