import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Icon from "@mdi/react";
import { mdiAccount, mdiLock, mdiEmail } from "@mdi/js";
import * as Yup from "yup";

// CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import history from "~/shared/services/history";
import { useAuth } from "~/modules/auth/hooks";
import logo from "~/shared/assets/custom/logo@2x.png";
import { Button, Checkbox, Input, Trans } from "~/shared/components/atoms";
import { signUpRequest } from "~/modules/auth/store/auth/actions";
import { DialogModal, Form } from "~/shared/components/molecules";

export default function SignUp() {
  const dispatch = useDispatch();

  // REDUCERS
  const isSigningUp = useSelector(state => state.auth.isSigningUp);

  const {
    REACT_APP_NAME,
    REACT_APP_USE_TERMS,
    REACT_APP_PRIVACY_POLICY,
  } = process.env;

  const hasClientTerms = REACT_APP_USE_TERMS || REACT_APP_PRIVACY_POLICY;

  // STATES
  const { isRegistrationEnabled } = useAuth();
  const [isTermsChecked, setIsTermsChecked] = React.useState(false);
  const [isClientTermsChecked, setIsClientTermsChecked] = React.useState(
    !hasClientTerms
  );
  const [showAlert, setShowAlert] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  const handleSubmit = data => {
    if (isTermsChecked && isClientTermsChecked) {
      dispatch(
        signUpRequest(data.name, String(data.email).trim(), data.password)
      );
    } else {
      setShowAlert(true);
    }
  };

  if (!isRegistrationEnabled) history.push("/login");

  return (
    <>
      <img src={logo} alt="logo" className="auth-logo" />

      <Form
        onSubmit={data => handleSubmit(data)}
        yupValidation={{
          name: Yup.string().required(translate("handlers.name.isRequired")),
          email: Yup.string()
            .email(translate("handlers.email.isEmailInvalid"))
            .trim()
            .required(translate("handlers.email.isRequired")),
          password: Yup.string().required(
            translate("handlers.password.isRequired")
          ),
        }}
      >
        <h3>
          <Trans translationKey="pages.signUp.title">
            Cadastre-se para assistir ao evento
          </Trans>
        </h3>

        <Input
          className="auth-input"
          title={translate("pages.signUp.nameInput.title")}
          name="name"
          placeholder={translate("pages.signUp.nameInput.placeholder")}
          icon={<Icon path={mdiAccount} />}
        />

        <Input
          className="auth-input"
          title={translate("pages.signUp.emailInput.title")}
          name="email"
          placeholder={translate("pages.signUp.emailInput.placeholder")}
          icon={<Icon path={mdiEmail} />}
          inputMode="email"
          autoCorrect="off"
          autoCapitalize="none"
        />

        <Input
          type="password"
          className="auth-input"
          title={translate("pages.signUp.passwordInput.title")}
          name="password"
          placeholder={translate("pages.signUp.passwordInput.placeholder")}
          icon={<Icon path={mdiLock} />}
        />

        <div className="auth-checkbox">
          <Checkbox
            data-testid="checkbox"
            onClick={() => setIsTermsChecked(!isTermsChecked)}
            checked={isTermsChecked}
          />
          <p className="auth-terms">
            <Trans translationKey="common.yazoTerms.part1">
              Declaro que li e aceito os
            </Trans>

            <a
              rel="noopener noreferrer"
              target="_blank"
              className="auth-button-terms"
              href="https://www.yazo.com.br/termos-de-uso-yazo/"
            >
              <Trans translationKey="common.yazoTerms.part2">
                Termos e Condições, e nossa Política de Privacidade
              </Trans>
            </a>
          </p>
        </div>

        {hasClientTerms && (
          <div className="auth-checkbox">
            <Checkbox
              data-testid="checkbox"
              onClick={() => setIsClientTermsChecked(!isClientTermsChecked)}
              checked={isClientTermsChecked}
            />
            <p className="auth-terms">
              <Trans translationKey="common.clientTerms.part1">
                Declaro que li e aceito os
              </Trans>

              {REACT_APP_USE_TERMS && (
                <a
                  rel="noopener noreferrer"
                  target="_blank"
                  className="auth-button-terms"
                  href={REACT_APP_USE_TERMS}
                >
                  <Trans translationKey="common.terms">
                    Termos e Condições
                  </Trans>{" "}
                  {!REACT_APP_PRIVACY_POLICY && REACT_APP_NAME}
                </a>
              )}
              {REACT_APP_PRIVACY_POLICY && (
                <>
                  ,
                  <a
                    rel="noopener noreferrer"
                    target="_blank"
                    className="auth-button-terms"
                    href={REACT_APP_PRIVACY_POLICY}
                  >
                    <Trans translationKey="common.privacyPolicy">
                      Política de Privacidade
                    </Trans>{" "}
                    {REACT_APP_NAME}
                  </a>
                </>
              )}
            </p>
          </div>
        )}

        <Button className="auth-button" type="submit" loading={isSigningUp}>
          <Trans translationKey="pages.signUp.submitButton">
            Fazer Cadastro
          </Trans>
        </Button>
      </Form>

      <footer className="auth-footer" style={{ height: 40 }}>
        <Link to="/login">
          <Trans translationKey="pages.signUp.signInLink">Fazer login</Trans>
        </Link>
      </footer>

      <DialogModal
        show={showAlert}
        title={translate("pages.signIn.alert.title")}
        description={translate("pages.signIn.alert.description")}
        onClose={() => setShowAlert(false)}
        onConfirm={() => setShowAlert(false)}
        onlyConfirm
        confirmButton="Entendi"
      />
    </>
  );
}
