import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  createGroup,
  updateGroup,
  deleteGroup,
} from "~/modules/signed/modules/cms/modules/admin/store/groups/actions";

// CUSTOM
import { popState } from "~/shared/services/history";
import { AdminFormModal as FormModal } from "~/modules/signed/modules/cms/components/AdminFormModal";
import { isDataValid } from "~/shared/utils/validations";
import { AdminTranslateInput as TranslateInput } from "../../AdminTranslateInput";

import { useTranslation } from "~/shared/hooks/translation";

function GroupModal({ group }) {
  const dispatch = useDispatch();
  const { isCreating, isUpdating, isDeleting } = useSelector(
    state => state.adminGroups.list
  );

  // TRANSLATE
  const { translate } = useTranslation();

  // STATES
  const editMode = isDataValid(group);
  const [formData, setFormData] = React.useState({});

  // FUNCTIONS
  React.useEffect(() => {
    if (editMode) setFormData(group);
  }, [editMode, group]);

  const handleSubmit = data => {
    if (editMode) {
      dispatch(updateGroup(data, group.id));
    } else {
      dispatch(createGroup(data));
    }
  };

  const handleDelete = () => {
    if (editMode) dispatch(deleteGroup(group.id));
  };

  React.useMemo(() => {
    if (
      isCreating === "FINISH" ||
      isDeleting === "FINISH" ||
      isUpdating === "FINISH"
    )
      popState();
  }, [isCreating, isDeleting, isUpdating]);

  return (
    <FormModal
      show
      title={
        editMode
          ? translate("entities.groups.editGroup")
          : translate("entities.groups.createGroup")
      }
      initialData={formData}
      yupValidation={{
        title: Yup.string().required(translate("handlers.name.isRequired")),
      }}
      onClose={popState}
      onSubmit={handleSubmit}
      enableDeleteButton={editMode}
      onDelete={handleDelete}
      isDeleting={isDeleting === "PENDING"}
      isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
    >
      <TranslateInput
        name="title"
        title={translate("entities.groups.editGroup")}
        placeholder={translate("entities.groups.firstPlaceholderGroup")}
      />
    </FormModal>
  );
}

GroupModal.propTypes = {
  group: PropTypes.object.isRequired,
};

export default GroupModal;
