import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const badgePropTypes = {
  /** The size in <b>px</b> for this component. Default value is 12 */
  size: PropTypes.number,

  /** Add an counter inside the <b>Badge</b>. */
  counter: PropTypes.number,

  /** If <b>true</b> add box shadow to this component */
  withBoxShadow: PropTypes.bool,

  /** Control the color for the <b>Badge</b> */
  color: PropTypes.string,

  /** Control the color for the <b>Badge</b> outline, default is <b>surface</b> color. */
  outlineColor: PropTypes.string,

  /** If <b>true</b> disable the <b>Badge</b>  outline */
  disableOutline: PropTypes.bool,

  variant: PropTypes.oneOf(["primary", "accent", "online"]),

  borderRadius: PropTypes.number,
};

const badgeDefaultProps = {
  size: 12,
  counter: null,
  withBoxShadow: false,
  color: null,
  disableOutline: false,
  outlineColor: null,
  variant: "online",
  borderRadius: null,
};

/** Just an simple Badge to use in any component. */
const Badge = ({
  variant,
  size,
  color,
  counter,
  withBoxShadow,
  borderRadius,
  outlineColor,
  disableOutline,
  ...rest
}) => {
  //* FUNCTIONS
  const renderCounter = () => {
    if (!counter) return null;

    if (counter > 9) return "+9";

    return counter;
  };

  return (
    <Container
      borderRadius={borderRadius}
      variant={variant}
      color={color}
      size={size}
      withBoxShadow={withBoxShadow}
      disableOutline={disableOutline}
      outlineColor={outlineColor}
      counter={counter}
      {...rest}
    >
      {renderCounter()}
    </Container>
  );
};

Badge.propTypes = badgePropTypes;

Badge.defaultProps = badgeDefaultProps;

export { Badge, badgePropTypes, badgeDefaultProps };
