import React from "react";
import { useParams } from "react-router-dom";

//* APIs
import {
  getUserExtractPointsHistoric,
  getUserPoints,
} from "../../../../apis/gamification";

//* CUSTOM IMPORTS
import { API_STATUS } from "~/shared/utils/apis";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { ExtractPointsLog } from "~/modules/signed/components/gamification/ExtractPointsLog";
import { openAdminGamification } from "~/shared/routes/navigation";
import { Container } from "./styles";
import { GamificationInfos } from "~/modules/signed/components/gamification/GamificationInfos";

const UserExtract = () => {
  const { userId } = useParams();
  const { setBreadCrumbs } = useCMS();
  const { translate } = useTranslation();

  //* STATES
  const [userPoint, setUserPoint] = React.useState(null);
  const [extractPoints, setExtractPoitns] = React.useState({
    data: [],
    page: 1,
    lastPage: 1,
    isLoaded: false,
  });
  const [apiStatus, setApiStatus] = React.useState(API_STATUS.IDLE);

  //* FUNCTIONS
  React.useEffect(() => {
    if (userId)
      getUserPoints({ userId }).then(response => {
        setUserPoint(response.data);
      });
  }, [userId]);

  React.useEffect(() => {
    if (userId)
      getUserExtractPointsHistoric({ userId, page: 1 }).then(response => {
        setExtractPoitns({ ...response.data, isLoaded: true });
      });
  }, [userId]);

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.gamification.singular"),
        function: () => openAdminGamification({ tabIndex: 2 }),
      },
      {
        id: 2,
        name: translate("entities.gamification.pointsExtract"),
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, translate]);

  const onPaginate = () => {
    setApiStatus(API_STATUS.PAGING);

    if (extractPoints.page < extractPoints.lastPage)
      getUserExtractPointsHistoric({ userId, page: extractPoints.page + 1 })
        .then(response => {
          setExtractPoitns(state => ({
            ...state,
            ...response.data,
            data: [...state.data, ...response.data.data],
          }));

          setApiStatus(API_STATUS.SUCCESS);
        })
        .catch(() => setApiStatus(API_STATUS.FAILED));
  };

  return (
    <Container>
      <GamificationInfos
        position={userPoint && userPoint.rank_position}
        points={userPoint && userPoint.points}
        userName={userPoint && userPoint.name}
        avatar={{ src: userPoint && userPoint.avatar, size: 68 }}
        isLoading={!userPoint}
      />

      <ExtractPointsLog
        pointsLog={extractPoints}
        isLoading={!extractPoints.isLoaded}
        isPaging={apiStatus === API_STATUS.PAGING}
        loaderListSize={9}
        maxHeight="calc(100vh - 160px)"
        onPaginate={onPaginate}
      />
    </Container>
  );
};

export default UserExtract;
