import React from "react";
import { useSelector, useDispatch } from "react-redux";

// ACTIONS
import {
  receiveCategories,
  paginateBroadcastRoom,
} from "~/modules/signed/modules/client/store/broadcastRooms/actions";

// APIs
import {
  listBroadcastCategories,
  listBroadcastRooms,
} from "~/modules/signed/modules/client/apis/broadcastRooms";

// CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { Typography, Box, Trans, Icon } from "~/shared/components/atoms";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { Empty, CellList } from "~/shared/components/molecules";
import LiveBroadcastRoomCell from "~/shared/components/organisms/cells/LiveBroadcastRoomCell";
import BroadcastRoomList from "~/modules/signed/modules/client/components/lists/BroadcastRoomList";
import LiveBroadcastRoomList from "./components/LiveBroadcastRoomSlider";
import { isListValid } from "~/shared/utils/validations";
import { Container, SearchBar } from "./styles";
import { openBroadcastRoom } from "~/shared/routes/navigation";

function BroadcastRoomCategoriesList() {
  const dispatch = useDispatch();

  // STATES
  const { data: categories, isLoaded } = useSelector(
    state => state.broadcastRooms.categories
  );
  const [isSearching, setIsSearching] = React.useState(false);
  const [search, setSearch] = React.useState("");
  const [isPaging, setIsPaging] = React.useState(false);

  // FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    const list = async () => {
      await listBroadcastCategories({ search: "", page: 1 }).then(response =>
        dispatch(receiveCategories({ broadcastRoomCategories: response.data }))
      );
    };

    list();
  }, [dispatch]);

  const handleSearch = async s => {
    setIsSearching(true);

    await listBroadcastCategories({ search: s }).then(response => {
      let auxCategories = response.data;

      //* *Sort array only if search has some data */
      if (s !== "") {
        const sortedArray = auxCategories.sort((a, b) => {
          if (
            isListValid(a.broadcastRooms.data) &&
            !isListValid(b.broadcastRooms.data)
          ) {
            return -1;
          }
          if (
            isListValid(b.broadcastRooms.data) &&
            !isListValid(a.broadcastRooms.data)
          ) {
            return 1;
          }
          return 0;
        });

        auxCategories = sortedArray;
      }

      dispatch(receiveCategories({ broadcastRoomCategories: auxCategories }));
    });

    setIsSearching(false);
  };

  const onPaginateClick = async (categoryId, page) => {
    setIsPaging(true);

    await listBroadcastRooms({
      page: page + 1,
      onLive: false,
      categoryId,
    }).then(response => {
      dispatch(
        paginateBroadcastRoom({ broadcastRooms: response.data, categoryId })
      );
    });

    setIsPaging(false);
  };

  const hasOnlyOneBroadcastRoom = () => {
    if (categories.length === 1)
      if (categories[0].broadcastRooms.data.length === 1) return true;

    return false;
  };

  const rendersWhenMoreThanOne = component => {
    if (isLoaded) if (!hasOnlyOneBroadcastRoom()) return component;

    return null;
  };

  const isTheOnlyRoomAllowed = () => {
    if (hasOnlyOneBroadcastRoom()) {
      const { room } = categories[0].broadcastRooms.data[0];

      return room.allow_access;
    }

    return false;
  };

  const renderHeaderComponent = () => {
    if (!isLoaded) return null;

    return (
      <Box width="100%" marginTop="40px" marginBottom="30px">
        <Typography className="bigTitle centered" style={{ marginBottom: 20 }}>
          {isTheOnlyRoomAllowed()
            ? categories[0].name
            : translate("entities.broadcastRooms.all")}
        </Typography>

        {!hasOnlyOneBroadcastRoom() && (
          <SearchBar
            centerPlaceholder
            variant="contained"
            placeholder={translate("entities.broadcastRooms.search")}
            onChange={value => {
              setSearch(value);
              handleSearch(value);
            }}
            isSearching={isSearching}
          />
        )}
      </Box>
    );
  };

  const renderCell = (category, index) => {
    const { page, data: broadcastRooms, lastPage } = category.broadcastRooms;

    if (hasOnlyOneBroadcastRoom()) {
      const {
        id,
        room,
        __meta__,
        open_live: openLive,
        end_live: endLive,
        video_player: videoPlayer,
        type,
        on_live: onLive,
      } = broadcastRooms[0];

      const isAllowed = room.allow_access;

      //* Only return this component if the room access is allowed
      if (isAllowed)
        return (
          <LiveBroadcastRoomCell
            style={{ margin: "0 auto" }}
            onClick={() =>
              openBroadcastRoom({ broadcastRoomId: id, title: room.title })
            }
            videoPlayer={{
              src: videoPlayer,
              type,
              plyrId: `liveBroadcastRoom${id}`,

              showThumbnail: false,
              onLive,

              //* *Disable autoPlay if Pip is disabled */
              autoPlay: false,
            }}
            broadcastRoomCell={{
              title: room.title,
              image: room.logo,
              startDate: openLive,
              endDate: endLive,
              speakers: __meta__.speakers,
              tags: room.tags,

              isBlocked: !room.allow_access,
              bottomTags: {
                trim: true,
                tags:
                  isListValid(room.partners) &&
                  room.partners.map(partner => ({
                    name: partner.title,
                  })),
              },
            }}
          />
        );
    }

    return (
      <BroadcastRoomList
        key={category.id}
        isLastOne={index === categories.length - 1}
        onPaginateClick={() => onPaginateClick(category.id, page)}
        pagination={{
          isPaging,
          hasNextPage: page < lastPage,
        }}
        title={category.name}
        broadcastRooms={broadcastRooms}
        isLoaded={!isSearching}
      />
    );
  };

  const renderCellLoader = () => (
    <div
      style={{
        display: "flex",
        flexWrap: "wrap",
        gap: 20,
        marginBottom: 20,
      }}
    >
      <CellLoader />
      <CellLoader />
      <CellLoader />
    </div>
  );

  return (
    <Container>
      {rendersWhenMoreThanOne(<LiveBroadcastRoomList />)}

      <CellList
        headerComponent={renderHeaderComponent()}
        orientation="column"
        data={categories}
        isLoaded={isLoaded}
        renderCell={renderCell}
        renderCellLoader={renderCellLoader}
        loaderListSize={2}
        emptyComponent={
          <Empty
            variant={search && "search"}
            search={search}
            icon={<Icon iconName="broadcastRooms" />}
            message={
              <Trans translationKey="entities.broadcastRooms.empty.message" />
            }
            description={
              <Trans translationKey="entities.broadcastRooms.empty.description" />
            }
          />
        }
      />
    </Container>
  );
}

export default BroadcastRoomCategoriesList;
