import types from "./types";

// LIST
export function listNetworkRooms() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listNetworkSuccessful(networkRooms) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { networkRooms },
  };
}

// SHOW
export function showNetworkRooms(networkRoomId) {
  return {
    type: types.SHOW.REQUEST,
    payload: { networkRoomId },
  };
}
export function showNetworkSuccessful(networkRoom) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { networkRoom },
  };
}

// CREATE
export function createNetworkRoom(networkRoom) {
  return {
    type: types.CREATE.REQUEST,
    payload: { networkRoom },
  };
}
export function createNetworkRoomSuccessful(networkRoom) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { networkRoom },
  };
}
export function createNetworkRoomFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateNetworkRoom(networkRoom, networkRoomId) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { networkRoom, networkRoomId },
  };
}
export function updateNetworkRoomSuccessful(networkRoom) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { networkRoom },
  };
}

export function updateNetworkRoomFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteNetworkRoom(networkRoomId) {
  return {
    type: types.DELETE.REQUEST,
    payload: { networkRoomId },
  };
}
export function deleteNetworkRoomSuccessful(networkRoomId) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { networkRoomId },
  };
}
export function deleteNetworkRoomFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// FORM
export function clearNetworkRoomForm() {
  return {
    type: types.FORM.CLEAR,
  };
}
