import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getUsersReportCountOnline } from "~/modules/signed/modules/cms/modules/admin/apis/users";

//* CUSTOM IMPORTS
import { BarChart } from "~/shared/components/organisms/BarChart";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";
import { DashboardReportCard } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function OnlineCard({ dateInterval }) {
  //* STATES
  const { status, data: userOnlineReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getUsersReportCountOnline({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardReportCard
      style={{
        position: "relative",
        padding: 16,
      }}
      accentTheme
      title={translate("common.onlineNow")}
      isLoaded={isLoaded}
      orientation="row"
      reportItems={[
        {
          id: 1,
          counter: isLoaded && userOnlineReport.online_users,
          description: translate("entities.users.plural"),
        },
      ]}
    >
      {isLoaded && (
        <BarChart
          variant="white"
          chartData={userOnlineReport.chartData}
          granularity={userOnlineReport.granularity}
          tipDescription={translate("common.accesses")}
        />
      )}
    </DashboardReportCard>
  );
}

OnlineCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
};

export default OnlineCard;
