import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Tab, Tabs, Content } from "./styles";

const staticTabsPropTypes = {
  /** Tabs object type */
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      error: PropTypes.bool,
    })
  ).isRequired,

  className: PropTypes.string,

  /** Childrens from each tabs */
  children: PropTypes.any.isRequired,
  onChange: PropTypes.func,
};

const staticTabsDefaultProps = {
  className: "swipe-tabs",
  onChange: () => {},
};

/** Static tabs is commonly used for tab navigation.
 *
 * <a href="https://material-ui.com/pt/api/tabs/#tabs-api"> click here to see detailed documentation</a> */
function StaticTabs({ className, tabs, children, onChange }) {
  // STATES
  const [index, setIndex] = React.useState(0);

  const handleChange = (event, newValue) => {
    onChange(newValue);
    setIndex(newValue);
  };

  const renderChildren = () => {
    let child;

    React.Children.map(children, (c, i) => {
      if (index === i) child = c;
    });

    return child;
  };

  return (
    <Container className={className}>
      <Tabs
        className="tabs"
        variant="scrollable"
        value={index}
        onChange={handleChange}
      >
        {tabs.length > 0 &&
          tabs.map(tab => (
            <Tab key={tab.id} label={tab.name} isErrored={tab.error} />
          ))}
      </Tabs>

      <Content>{renderChildren()}</Content>
    </Container>
  );
}

StaticTabs.propTypes = staticTabsPropTypes;

StaticTabs.defaultProps = staticTabsDefaultProps;

export { StaticTabs, staticTabsPropTypes, staticTabsDefaultProps };
