import React from "react";
import PropTypes from "prop-types";
import { mdiDrag } from "@mdi/js";
import { MdMoreVert } from "react-icons/md";
import Icon from "@mdi/react";
import { useDispatch, useSelector } from "react-redux";
import { ThemeContext } from "styled-components";

// ACTIONS
import { deleteBanner } from "~/modules/signed/modules/cms/modules/admin/store/banners/actions";

// CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { Container, Banner, Title, Url, Options } from "./styles";
import { DialogModal } from "~/shared/components/molecules";

function BannerCell({ banner, onEdit }) {
  const { translate } = useTranslation();

  const { colors } = React.useContext(ThemeContext);
  const { image, url, title } = banner;
  const dispatch = useDispatch();
  const { isDeleting } = useSelector(state => state.adminBanners.list);

  // STATES
  const [showModal, setShowModal] = React.useState(false);

  // FUNCTIONS
  function fillItems() {
    const items = [
      {
        id: 1,
        name: translate("entities.banners.edit"),
        function: () => onEdit(banner),
      },
      {
        id: 2,
        name: translate("entities.banners.delete"),
        function: () => setShowModal(true),
        color: colors.redWrong,
      },
    ];

    return items;
  }

  return (
    <>
      <Container>
        <Banner src={image} />

        <footer>
          <Icon path={mdiDrag} id="banner-cell-grab" />

          <div id="banner-cell-info">
            <Title>{title}</Title>
            <Url>{url}</Url>
          </div>

          <Options fillItems={fillItems} buttonIcon={<MdMoreVert />} />
        </footer>
      </Container>

      <DialogModal
        show={showModal}
        title={translate("entities.banners.delete")}
        description={`${translate("entities.banners.deleteMessage")} ${
          banner.title
        }`}
        onClose={() => setShowModal(false)}
        onCancel={() => setShowModal(false)}
        onConfirm={() => dispatch(deleteBanner({ bannerId: banner.id }))}
        isLoading={isDeleting === "PENDING"}
      />
    </>
  );
}

BannerCell.propTypes = {
  banner: PropTypes.object.isRequired,
  onEdit: PropTypes.func,
};

BannerCell.defaultProps = {
  onEdit: () => {},
};

export default BannerCell;
