import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { tooltipPropTypes } from "../../../../atoms";
import { Container, Typography, Tooltip } from "./styles";

const tableRowPropTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      error: PropTypes.bool,
      onClick: PropTypes.func,
    })
  ).isRequired,

  /** Default variant, colored */
  variant: PropTypes.oneOf(["withLines", "colored", "title"]),

  /** Define the size of the row, default is "small" */
  size: PropTypes.oneOf(["small", "regular", "large"]),

  /** Render an component only if the value of the current row is not valid */
  emptyValueComponent: PropTypes.element,

  /** If this props is implemented render an <b>Tooltip</b> in the end of this component,
   * default is null
   */
  tooltip: PropTypes.shape(tooltipPropTypes),

  /** Informs to the <b>TableRow</b> that is the last Row inside the Table
   *
   * If the variant is "withLines" remove the line when this props is TRUE
   */
  isTheLastOne: PropTypes.bool,

  /** Arary of width in percentage for each colum.
   *
   * Example: ['30%', '70%]
   */
  columnSizes: PropTypes.arrayOf(PropTypes.string),
};

const tableRowDefaultProps = {
  variant: "colored",
  size: "regular",
  emptyValueComponent: null,
  tooltip: null,
  isTheLastOne: false,
  columnSizes: [],
};

const TableRow = ({
  data,
  emptyValueComponent,
  tooltip,
  variant,
  isTheLastOne,
  size,
  columnSizes,
  ...rest
}) => {
  return (
    <Container
      size={size}
      isTheLastOne={isTheLastOne}
      variant={variant}
      {...rest}
    >
      {data &&
        data.map((item, index) => {
          const { error, value, onClick } = item;

          const auxIndex = index;

          return (
            <Typography
              maxLines={1}
              key={`${auxIndex}-${value}`}
              className="table-row-value"
              onClick={() => onClick && onClick(item)}
              error={error}
              style={{ display: "flex", alignItems: "center" }}
              width={columnSizes[index]}
            >
              {value || emptyValueComponent}
            </Typography>
          );
        })}

      {tooltip && <Tooltip {...tooltip} />}
    </Container>
  );
};

TableRow.propTypes = tableRowPropTypes;

TableRow.defaultProps = tableRowDefaultProps;

export { TableRow, tableRowPropTypes, tableRowDefaultProps };
