import React from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";

//* APIs
import { updateUserScore } from "../../../../apis/gamification";

//* CUSTOM IMPORTS
import { NumericInput } from "~/shared/components/molecules";
import { FormModal, Dropdown } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminGamification } from "../../hooks";
import { GamificationInfos } from "~/modules/signed/components/gamification/GamificationInfos";

const editUserScoreModalPropTypes = {
  userRanking: PropTypes.shape({
    position: PropTypes.number,
    avatar: PropTypes.string,
    name: PropTypes.name,
    points: PropTypes.number,
    user_id: PropTypes.number,
  }).isRequired,
  onClose: PropTypes.func.isRequired,
};

const EditUserScoreModal = ({ userRanking, onClose, ...rest }) => {
  const { translate } = useTranslation();
  const { updateUserRankingScore } = useAdminGamification();

  //* STATES
  const [isUpdating, setIsUpdating] = React.useState(false);
  const [operationType, setOperationType] = React.useState("add");

  const OPERATION_TYPES = [
    {
      id: 0,
      name: translate("common.add"),
      value: "add",
    },
    {
      id: 1,
      name: translate("common.subtract"),
      value: "subtract",
    },
  ];

  //* FUNCTIONS
  const handleOnUpdateUserScore = async data => {
    const currentScore = userRanking.points;

    /** Validation if admin try to subtract more thant current score */
    if (operationType === "subtract")
      if (data.score > currentScore) {
        toast.error(
          `${translate(
            "entities.gamification.maxSubtractScore.part1"
          )} ${currentScore} ${translate(
            "entities.gamification.maxSubtractScore.part2"
          )}`
        );

        return;
      }

    setIsUpdating(true);

    const userId = userRanking.user_id;

    const scoreIncrement = operationType === "add" ? data.score : -data.score;
    const newScore = Number(currentScore) + Number(scoreIncrement);

    await updateUserScore(userId, scoreIncrement).then(() => {
      setIsUpdating(false);
      updateUserRankingScore(newScore, userId);
      onClose();
    });
  };

  return (
    <FormModal
      {...rest}
      onSubmit={handleOnUpdateUserScore}
      onClose={onClose}
      title={translate("entities.gamification.editScore")}
      style={{ maxWidth: 500 }}
      isLoading={isUpdating}
    >
      <GamificationInfos
        position={userRanking.position}
        points={userRanking.points}
        userName={userRanking.name}
        avatar={{ src: userRanking.avatar, size: 48 }}
      />

      <Dropdown
        name="operationType"
        options={OPERATION_TYPES}
        title={`${translate("common.add")} ${translate(
          "common.or"
        )} ${translate("common.subtract")}`}
        initialValue={translate("common.add")}
        style={{ marginBottom: 16, width: "100%" }}
        onChange={value => setOperationType(value)}
      />

      <NumericInput title={translate("common.amountOfPoints")} name="score" />
    </FormModal>
  );
};

EditUserScoreModal.propTypes = editUserScoreModalPropTypes;

export { EditUserScoreModal };
