import i18n from "../../translation/i18n";

/** An array of static time options to be used inside Dropdowns. */
export const TIME_OPTIONS = [
  {
    id: 0,
    name: `5 ${i18n.t("common.minutes")}`,
    value: 5,
  },
  {
    id: 1,
    name: `15 ${i18n.t("common.minutes")}`,
    value: 15,
  },
  {
    id: 2,
    name: `30 ${i18n.t("common.minutes")}`,
    value: 30,
  },
  {
    id: 3,
    name: `1 ${i18n.t("common.hour")}`,
    value: 60,
  },
];

/** This function will return the name of an TIME_OPTIONs based on its value. */
export const getKeySelectValue = (
  searchKey,
  searchValue,
  selectOptions,
  extractKey
) => {
  const findedOption = selectOptions.find(
    option => option[searchKey] === searchValue
  );

  if (findedOption) return findedOption[extractKey];
};
