import React from "react";
import PropTypes from "prop-types";
import { MdKeyboardArrowDown } from "react-icons/md";

// NAVIGATION
import { openMarketRoom, openBroadcastRoom } from "~/shared/routes/navigation";

// CUSTOM IMPORTS
import { isListValid } from "~/shared/utils/validations";
import { Typography, Trans, Icon } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import CellLoader from "~/shared/components/molecules/cellLoaders/BroadcastCellLoader";
import { UserBroadcastRoomCell } from "../../cells/UserBroadcastRoomCell";
import { Header, Pagination } from "./styles";

const broadcastRoomListPropTypes = {
  broadcastRooms: PropTypes.arrayOf(PropTypes.object).isRequired,
  isLoaded: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  categoryId: PropTypes.number.isRequired,
  onPaginateClick: PropTypes.func,
  isLastOne: PropTypes.bool,
  search: PropTypes.bool,

  pagination: PropTypes.shape({
    isPaging: PropTypes.bool,
    hasNextPage: PropTypes.bool,
  }),
};

const broadcastRoomListDefaultProps = {
  isLastOne: false,
  pagination: null,
  onPaginateClick: () => {},
  search: false,
};

function BroadcastRoomList({
  broadcastRooms,
  isLoaded,
  title,
  onPaginateClick,
  isLastOne,
  pagination,
  search,
  ...rest
}) {
  return (
    <CellList
      {...rest}
      headerComponent={
        <Header>
          <Typography className="accent bold title">{title}</Typography>
        </Header>
      }
      isPaging={pagination && pagination.isPaging}
      emptyComponent={
        <Empty
          variant={search && "search"}
          search={search}
          icon={<Icon iconName="broadcastRooms" />}
          message={
            <Trans translationKey="entities.broadcastRooms.empty.message" />
          }
          description={
            <Trans translationKey="entities.broadcastRooms.empty.description" />
          }
        />
      }
      data={broadcastRooms}
      isLoaded={isLoaded}
      renderCell={broadcastRoom => {
        const {
          room,
          __meta__,
          open_live: openLive,
          end_live: endLive,
          on_live,
        } = broadcastRoom;

        return (
          <UserBroadcastRoomCell
            onClick={() =>
              openBroadcastRoom({
                broadcastRoomId: broadcastRoom.id,
                title: room.title,
              })
            }
            id={room.id}
            isBookmarked={room.bookmark}
            image={room.logo}
            startDate={openLive}
            endDate={endLive}
            title={room.title}
            speakers={__meta__.speakers}
            tags={room.tags}
            bottomTags={{
              tags:
                isListValid(room.partners) &&
                room.partners.map(partner => ({
                  name: partner.title,
                  onClick: () => openMarketRoom(partner.id, partner.title),
                  hoverable: true,
                })),
            }}
            onLive={on_live}
            isBlocked={!room.allow_access}
          />
        );
      }}
      renderCellLoader={() => <CellLoader />}
      loaderListSize={3}
      gap={20}
      footerComponent={
        pagination && (
          <Pagination isLastOne={isLastOne}>
            {pagination.hasNextPage && (
              <button
                type="button"
                title="Carregar mais salas"
                onClick={onPaginateClick}
              >
                <Trans translationKey="common.showMore">Mostrar mais</Trans>
                <MdKeyboardArrowDown />
              </button>
            )}
          </Pagination>
        )
      }
    />
  );
}

BroadcastRoomList.propTypes = broadcastRoomListPropTypes;

BroadcastRoomList.defaultProps = broadcastRoomListDefaultProps;

export default BroadcastRoomList;
