import React from "react";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import PropTypes from "prop-types";

const draggableBoardPropTypes = {
  data: PropTypes.arrayOf(PropTypes.object),
  isDragDisabled: PropTypes.bool,
  renderCell: PropTypes.func.isRequired,
  renderHeaderGroup: PropTypes.func,
  groupGap: PropTypes.string,
  cellGap: PropTypes.string,
  renderFirstCell: PropTypes.func,
  onDragColumn: PropTypes.func,
  onDragItemToColumn: PropTypes.func,
  onMoveItemOfColumn: PropTypes.func,
};

const draggableBoardDefaultProps = {
  groupGap: null,
  cellGap: null,
  onDragColumn: () => {},
  renderFirstCell: () => {},
  onDragItemToColumn: () => {},
  renderHeaderGroup: () => {},
  onMoveItemOfColumn: () => {},
  isDragDisabled: false,
  data: [],
};

const DraggableBoard = ({
  data,
  onDragColumn,
  renderHeaderGroup,
  onDragItemToColumn,
  onMoveItemOfColumn,
  renderFirstCell,
  renderCell,
  groupGap,
  cellGap,
}) => {
  const onDragEnd = result => {
    const { destination, source, draggableId, type } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    if (type === "column") {
      const newColumnOrder = Array.from(data);
      const draggabledColumn = newColumnOrder.find(
        item => String(item.id) === String(draggableId)
      );

      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggabledColumn);

      onDragColumn(newColumnOrder);
      return;
    }

    const startColumn = data.findIndex(
      item => String(item.id) === String(source.droppableId)
    );

    const finishColumn = data.findIndex(
      item => String(item.id) === String(destination.droppableId)
    );

    if (startColumn === finishColumn) {
      //* MOVING ITEMS OF A COLUMN
      const dataFinishColumn = Array.from(
        data[startColumn].broadcastRooms.data
      );
      const draggableItem = dataFinishColumn.find(
        item => String(`broadcast-${item.id}`) === String(draggableId)
      );
      dataFinishColumn.splice(source.index, 1);
      dataFinishColumn.splice(destination.index, 0, draggableItem);

      onMoveItemOfColumn(dataFinishColumn, startColumn);

      return;
    }

    // * MOVING ITEMS FROM A COLUMN TO ANOTHER COLUMN
    const columnStartData = Array.from(data[startColumn].broadcastRooms.data);
    const draggableItem = columnStartData.find(
      item => String(`broadcast-${item.id}`) === String(draggableId)
    );
    columnStartData.splice(source.index, 1);

    const columnFinishData = Array.from(data[finishColumn].broadcastRooms.data);
    columnFinishData.splice(destination.index, 0, draggableItem);

    onDragItemToColumn(
      draggableItem,
      destination.droppableId,
      finishColumn,
      startColumn
    );
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="all-columns" direction="vertical" type="column">
        {provided => (
          <div
            style={{ display: "flex", flexDirection: "column", gap: groupGap }}
            {...provided.droppableProps}
            ref={provided.innerRef}
          >
            {data.map((column, index) => {
              const columns = column;
              const tasks = columns.broadcastRooms.data.map(task => task);
              return (
                <div key={columns.id} style={{ display: "flex" }}>
                  <Draggable draggableId={String(column.id)} index={index}>
                    {provided => (
                      <div {...provided.draggableProps} ref={provided.innerRef}>
                        <div {...provided.dragHandleProps}>
                          {renderHeaderGroup(column)}
                        </div>
                        <div>
                          <Droppable
                            droppableId={String(column.id)}
                            type="task"
                            direction="horizontal"
                          >
                            {provided => (
                              <div
                                style={{
                                  display: "flex",
                                  flexWrap: "wrap",
                                  gap: cellGap,
                                }}
                                ref={provided.innerRef}
                                {...provided.droppableProps}
                              >
                                {renderFirstCell(column)}
                                {tasks.map((task, index) => {
                                  return (
                                    <div key={task.id}>
                                      <Draggable
                                        draggableId={String(
                                          `broadcast-${task.id}`
                                        )}
                                        index={index}
                                      >
                                        {provided => (
                                          <div
                                            {...provided.draggableProps}
                                            {...provided.dragHandleProps}
                                            ref={provided.innerRef}
                                          >
                                            {renderCell(task, column)}
                                          </div>
                                        )}
                                      </Draggable>
                                    </div>
                                  );
                                })}
                                {provided.placeholder}
                              </div>
                            )}
                          </Droppable>
                        </div>
                      </div>
                    )}
                  </Draggable>
                </div>
              );
            })}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </DragDropContext>
  );
};

DraggableBoard.propTypes = draggableBoardPropTypes;

DraggableBoard.defaultProps = draggableBoardDefaultProps;

export { DraggableBoard, draggableBoardPropTypes, draggableBoardDefaultProps };
