import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Typography } from "../index";
import { Container, MaterialCheckbox } from "./styles";

const checkboxPropTypes = {
  className: PropTypes.string,

  /** Callback function when user click into checkbox */
  onClick: PropTypes.func,

  /** If TRUE fill checkbox body */
  checked: PropTypes.bool,

  /** An string to display in front of checkbox */
  placeholder: PropTypes.string,

  /** Control the stylesd of the <b>Container</b> of the <b>Checkbox</b> */
  style: PropTypes.object,
};

const checkboxDefaultProps = {
  className: null,
  onClick: () => {},
  checked: false,
  placeholder: null,
  style: null,
};

/** CheckBoxes allow users to complete tasks that involve making
 * choices such as selecting options, or switching settings on or off.
 * It provides a clear visual of either a true or false choice. */
function Checkbox({
  className,
  onClick,
  checked,
  placeholder,
  style,
  ...rest
}) {
  const { colors } = React.useContext(ThemeContext);

  const useStyles = makeStyles({
    root: {
      "&:active": {
        backgroundColor: colors.shadow,
      },
    },
  });

  const classes = useStyles();

  return (
    <Container className={className} style={style}>
      <MaterialCheckbox
        {...rest}
        checked={checked}
        onClick={onClick}
        className={classes.root}
        color="default"
      />

      {placeholder && (
        <Typography className="subTitle">{placeholder}</Typography>
      )}
    </Container>
  );
}

Checkbox.propTypes = checkboxPropTypes;

Checkbox.defaultProps = checkboxDefaultProps;

export { Checkbox, checkboxPropTypes, checkboxDefaultProps };
