import React, { createContext, useContext } from "react";
import { useSelector } from "react-redux";
import PropTypes from "prop-types";

//* APIs
import {
  listLanguages,
  changeLanguage as callChangeLanguage,
} from "../../apis/languages";

//* CUSTOM IMPORTS
import { useTranslation } from "~/shared/hooks/translation";
import { useAlert } from "~/shared/hooks/alert";

const DynamicTranslationContext = createContext(null);

const DynamicTranslationProvider = ({ children }) => {
  const { language: userLanguage } = useSelector(
    state => state.user.profile.data
  );
  const { changeLanguage: changeStaticLanguage } = useTranslation();

  //* STATES
  const [isLangLoaded, setIsLangLoaded] = React.useState(false);
  const [isChangingLang, setIsChangeLang] = React.useState(false);
  const [defaultLanguage, setDefaultLanguage] = React.useState(null);
  const [currentLanguage, setCurrentLanguage] = React.useState({
    title: null,
    countryCode: null,
    id: null,
    refCode: null,
    staticLanguage: null,
  });
  const [languages, setLanguages] = React.useState([]);

  //* FUNCTIONS
  const { translate } = useTranslation();
  const { showAlert } = useAlert();

  React.useEffect(() => {
    const list = async () => {
      await listLanguages().then(response => {
        const langs = response.data;

        setLanguages(langs);

        const defaultLang = response.data.find(lang => lang.is_default);

        if (defaultLang) setDefaultLanguage(defaultLang);

        if (response.data.length > 0) {
          const userLang = langs.find(lang => lang.ref_code === userLanguage);

          if (userLang) {
            const {
              title,
              flag_icon: countryCode,
              ref_code: refCode,
              static_language: staticLanguage,
            } = userLang;

            setCurrentLanguage({
              title,
              countryCode,
              refCode,
              staticLanguage,
            });
          }
        }

        setIsLangLoaded(true);
      });
    };

    list();
  }, [userLanguage]);

  /** This function returns the translated string based on the translation chosen by the user. */
  const dTranslate = React.useCallback(
    jsonTranslation => {
      if (!jsonTranslation) return "";

      const translationKey = Object.keys(jsonTranslation).find(
        key => key === currentLanguage.refCode
      );

      if (translationKey)
        if (jsonTranslation[translationKey])
          return jsonTranslation[translationKey];

      if (defaultLanguage) return jsonTranslation[defaultLanguage.ref_code];

      return "";
    },
    [currentLanguage.refCode, defaultLanguage]
  );

  const getTranslateByRefCode = React.useCallback(
    (jsonTranslation, refCode) => {
      if (!(jsonTranslation instanceof Object)) return;

      const translationKey = Object.keys(jsonTranslation).find(
        key => key === refCode
      );

      return translationKey ? jsonTranslation[translationKey] : "";
    },
    []
  );

  const updateTranslateByRefCode = React.useCallback(
    (value, jsonTranslation, refCode) => {
      if (!(jsonTranslation instanceof Object)) return;

      const auxJson = jsonTranslation;

      const key = Object.keys(jsonTranslation).find(k => k === refCode);

      auxJson[key] = value;

      return auxJson;
    },
    []
  );

  /** This function will craete an jsonTranslation based on languages, and will fill than */
  const createJsonTranslation = React.useCallback(
    jsonTranslation => {
      if (languages.length) {
        const formmatedJson = {};

        /** Create an JSON based on languages array and fill then with the API response */
        languages.forEach(lang => {
          const languageValue = jsonTranslation
            ? Object.values(jsonTranslation).find(
                (_, index) =>
                  Object.keys(jsonTranslation)[index] === lang.ref_code
              )
            : "";

          formmatedJson[lang.ref_code] = languageValue || "";
        });

        return formmatedJson;
      }

      return null;
    },
    [languages]
  );

  /** This function will change the current language */
  const handleChangeLanguage = React.useCallback(
    language => {
      const update = async () => {
        setCurrentLanguage(language);
        changeStaticLanguage(language.static_language);
        setIsChangeLang(true);

        await callChangeLanguage({ langRefCode: language.refCode })
          .then(() => setIsChangeLang(false))
          .catch(() => setIsChangeLang(false));
      };

      update();
    },
    [changeStaticLanguage]
  );

  const changeLanguage = React.useCallback(
    language => {
      if (!language) return;

      showAlert({
        title: translate("entities.languages.change"),
        description: (
          <>
            {translate("entities.languages.yourLanguageWillBeChanged")}{" "}
            <strong>{language.title}</strong>.{" "}
            {translate("common.doYouWishToContinue")}
          </>
        ),
        onConfirm: () => handleChangeLanguage(language),
      });
    },
    [handleChangeLanguage, showAlert, translate]
  );

  const fillLanguagesMenu = () =>
    languages.map(language => ({
      ...language,
      id: language.ref_code,
      name: language.title,
      countryCode: language.flag_icon,
      isDeletable: language.deletable,
    }));

  const getLangIdByRefCode = React.useCallback(
    refCode => {
      const language = languages.find(lang => lang.ref_code === refCode);

      return language ? language.id : null;
    },
    [languages]
  );

  const addLanguage = React.useCallback(
    language => setLanguages(list => [...list, language]),
    []
  );

  const updateLanguage = React.useCallback(
    (language, languageId) => {
      const { static_language: staticLanguage } = language;

      const index = languages.findIndex(l => l.id === languageId);

      if (currentLanguage.staticLanguage !== staticLanguage)
        changeStaticLanguage(staticLanguage);

      setLanguages(list => {
        list[index] = language;

        return list;
      });
    },

    [changeStaticLanguage, currentLanguage.staticLanguage, languages]
  );

  const removeLanguage = React.useCallback(languageId => {
    setLanguages(list => list.filter(language => language.id !== languageId));
  }, []);

  /** This functions convert an simple string into an jsonTranslation object */
  const toJsonTranslation = React.useCallback(
    string => {
      if (languages.length) {
        const object = {};

        languages.forEach(lang => {
          object[lang.ref_code] = string;
        });

        return object;
      }

      return string;
    },
    [languages]
  );

  /** Get the language name based on refCode */
  const getLangNameByRefCode = React.useCallback(
    refCode => {
      const lang = languages.find(l => l.ref_code === refCode);

      return lang ? lang.title : "";
    },
    [languages]
  );

  const dTranslateTags = React.useCallback(
    tags => {
      if (tags)
        return tags.map(tag => ({ ...tag, name: dTranslate(tag.name) }));
    },
    [dTranslate]
  );

  const dTranslatePartners = React.useCallback(
    partners => {
      if (partners)
        return partners.map(partner => ({
          name: dTranslate(partner.title),
        }));
    },
    [dTranslate]
  );

  const dTranslateSpeakers = React.useCallback(
    speakers => {
      if (speakers)
        return speakers.map(speaker => ({
          ...speaker,
          role: dTranslate(speaker.role),
          company: dTranslate(speaker.company),
          bio: dTranslate(speaker.bio),
        }));
    },
    [dTranslate]
  );

  return (
    <DynamicTranslationContext.Provider
      value={{
        languages,
        defaultLanguage,
        currentLanguage,
        isChangingLang,
        isLangLoaded,

        getTranslateByRefCode,
        updateTranslateByRefCode,
        createJsonTranslation,
        fillLanguagesMenu,
        dTranslate,
        dTranslateTags,
        dTranslatePartners,
        dTranslateSpeakers,
        getLangNameByRefCode,
        getLangIdByRefCode,
        toJsonTranslation,
        addLanguage,
        removeLanguage,
        updateLanguage,
        changeLanguage,
      }}
    >
      {children}
    </DynamicTranslationContext.Provider>
  );
};

DynamicTranslationProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

function useDynamicTranslation() {
  const context = useContext(DynamicTranslationContext);

  if (!context)
    throw new Error(
      "useDynamicTranslation must be used within DynamicTranslationProvider"
    );

  return context;
}

export { DynamicTranslationProvider, useDynamicTranslation };
