import api from "~/shared/services/api";

export const getAccessByBroadcastRoomExport = ({
  startDate,
  endDate,
  timezone,
}) =>
  Promise.resolve(
    api.get("admin/users/rooms/export", {
      params: {
        room_name: "broadcast-rooms",
        start_date: startDate,
        end_date: endDate,
        timezone,
      },
    })
  );

