import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";

//* APIs
import { isSameMinute } from "date-fns";
import {
  getSponsorMeetingConfig,
  createSponsorMeetingConfig,
  updateSponsorMeetingConfig,
} from "../../../apis/sponsors";

//* CUSTOM IMPORTS
import { useSponsorCMS } from "../../../hooks";
import { createDate, getFullDate, getFullHours } from "~/shared/utils/dates";
import { getAvailableTimeCourser } from "./utils/getAvailableTimeCourser";
import { getAvailableHours } from "./utils/getAvailableHours";
import { useTranslation } from "~/shared/hooks/translation";

const SponsorScheduleContext = createContext(null);

const MEETING_CONFIG_INITIAL_DATA = {
  meeting_time: "",
  break_time: "",
  startDate: "",
  endDate: "",
  startTime: "",
  endTime: "",
  daysWeek: [1, 2, 3, 4, 5],
};

function SponsorScheduleProvider({ children }) {
  const { managedPartnerId } = useSponsorCMS();

  const { translate } = useTranslation();

  //* STATES
  const [isSaving, setIsSaving] = React.useState(false);
  const [isFormLoading, setIsFormLoading] = React.useState(true);
  const [meetingConfig, setMeetingConfig] = React.useState(
    MEETING_CONFIG_INITIAL_DATA
  );
  const [blockedHours, setBlockedHours] = React.useState([]);

  /** Call an API to check if partner has an meeting Config */
  React.useEffect(() => {
    if (managedPartnerId) {
      setIsFormLoading(true);

      getSponsorMeetingConfig({ partnerId: managedPartnerId }).then(
        response => {
          if (response.data) {
            const {
              id,
              meeting_time,
              break_time,
              start_date: startDate,
              end_date: endDate,
              days_of_week,
              blocked_hours,
            } = response.data;

            setMeetingConfig({
              id,
              break_time,
              meeting_time,
              daysWeek: days_of_week.map(day => Number(day)),
              startDate: getFullDate(startDate),
              endDate: getFullDate(endDate),
              startTime: getFullHours(startDate),
              endTime: getFullHours(endDate),
            });

            setBlockedHours(blocked_hours);
          } else {
            /** If partner do not has an meeting config clear the meetingConfig state */
            setMeetingConfig(MEETING_CONFIG_INITIAL_DATA);
            setBlockedHours([]);
          }

          setIsFormLoading(false);
        }
      );
    }
  }, [managedPartnerId]);

  /** Format data to send to API */
  const formattedFormData = React.useMemo(() => {
    const {
      break_time,
      daysWeek,
      endDate,
      endTime,
      meeting_time,
      startDate,
      startTime,
    } = meetingConfig;

    const start_date = createDate({ date: startDate, time: startTime });
    const end_date = createDate({ date: endDate, time: endTime });

    return {
      break_time,
      meeting_time,
      days_of_week: daysWeek,
      start_date,
      end_date,
      partner_id: managedPartnerId,
      blocked_hours: blockedHours.map(blockedHour => ({
        start_date: new Date(blockedHour.start_date),
        end_date: new Date(blockedHour.end_date),
      })),
    };
  }, [meetingConfig, managedPartnerId, blockedHours]);

  /** This function will check if all of meetingConfig attributes are filled */
  const isFormFillyFilled = React.useCallback(
    () =>
      !Object.values(meetingConfig).some(value => {
        if (value instanceof Array) return !value.length;

        return !value;
      }),
    [meetingConfig]
  );

  const availableHours = React.useMemo(
    () => getAvailableHours(formattedFormData),
    [formattedFormData]
  );

  /** This constant store the availableTimeCourses based on availableHours */
  const availableTimeCourses = React.useMemo(
    () =>
      isFormFillyFilled()
        ? getAvailableTimeCourser(availableHours, translate)
        : [],
    [availableHours, isFormFillyFilled, translate]
  );

  /** Get only the blocked hour that is in form startDate and endDate range. */
  const getBlockedHourInAvailableHours = blocked_hours =>
    blocked_hours.filter(hour =>
      availableHours.some(h =>
        isSameMinute(new Date(hour.start_date), new Date(h.startDate))
      )
    );

  /** This function control witch API should be called when sabe the meeting config. */
  const saveMeetingConfig = () => {
    const { blocked_hours } = formattedFormData;

    const data = {
      ...formattedFormData /** Get only the blocked hour that is in form startDate and endDate range. */,
      blocked_hours: getBlockedHourInAvailableHours(blocked_hours),
    };

    setIsSaving(true);
    const saveMessage = translate("entities.meetings.saveMessage");

    /** If partner already has an meeting config call UPDATE API */
    if (meetingConfig.id) {
      updateSponsorMeetingConfig({
        data,
        meetingConfigId: meetingConfig.id,
      })
        .then(() => {
          toast.success(translate(saveMessage));
          setIsSaving(false);
        })
        .catch(() => setIsSaving(false));
    } else {
      /** If partner do not has an meeting config call the CREATE API */
      createSponsorMeetingConfig({ data })
        .then(() => {
          setIsSaving(false);
          toast.success(saveMessage);
        })
        .catch(() => setIsSaving(false));
    }
  };

  /** This function will store all Form values inside an object. */
  const setScheduleFormData = (key, value) => {
    /** If daysWeek has just 1 item selected do not unselect this item */
    if (key === "daysWeek" && value.length < 1) {
      toast.error(translate("entities.meetings.form.daysWeekError"));
      return;
    }

    setMeetingConfig(state => ({ ...state, [key]: value }));
  };

  const availableHoursSize = React.useMemo(() => {
    let hoursSize = 0;
    let hoursBlocked = 0;

    availableTimeCourses.forEach(timeCourse => {
      hoursSize += timeCourse.data.length;

      timeCourse.data.forEach(hour => {
        if (hour.isBlocked) hoursBlocked += 1;
      });
    });

    return hoursBlocked >= hoursSize - 1;
  }, [availableTimeCourses]);

  /** This function will block or unblock an hour in preview. */
  const handleBlockedHour = React.useCallback(
    (startDate, endDate) => {
      setBlockedHours(state => {
        const findedHourIndex = state.findIndex(hour =>
          isSameMinute(new Date(hour.start_date), startDate)
        );

        if (findedHourIndex !== -1) {
          state.splice(findedHourIndex, 1);
        } else if (availableHoursSize) {
          toast.error(translate("entities.toast.atLeastOneHourSelected"));
        } else {
          state.push({ start_date: startDate, end_date: endDate });
        }

        return [...state];
      });
    },
    [availableHoursSize, translate]
  );

  return (
    <SponsorScheduleContext.Provider
      value={{
        //* States
        isSaving,
        isFormLoading,
        meetingConfig,
        availableTimeCourses,
        blockedHours,

        //* Functions
        isFormFillyFilled,
        setScheduleFormData,
        saveMeetingConfig,
        handleBlockedHour,
      }}
    >
      {children}
    </SponsorScheduleContext.Provider>
  );
}

SponsorScheduleProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useSponsorSchedule() {
  const context = useContext(SponsorScheduleContext);

  if (!context)
    throw new Error(
      "useSponsorSchedule must be used within SponsorScheduleProvider"
    );

  return context;
}

export { SponsorScheduleProvider, useSponsorSchedule };
