export function convertViewType(viewType, male = false) {
  switch (viewType) {
    case ROOM_VIEW_TYPE.PUBLIC:
      return male ? "Público" : "Pública";

    case ROOM_VIEW_TYPE.PRIVATE:
      return male ? "Privado" : "Privada";

    case ROOM_VIEW_TYPE.HIDE:
      return male ? "Escondido" : "Escondida";

    default:
      return null;
  }
}

