import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { TagContainer } from "./styles";

const tagPropTypes = {
  variant: PropTypes.oneOf(["live", "contained", "outlined", "opaque"]),

  children: PropTypes.string.isRequired,

  /** Define the background tag color */
  color: PropTypes.string,

  /** Define the borderRadius tag color */
  borderRadius: PropTypes.number,

  /** If TRUE, add an hover effect, default is "false" */
  hoverable: PropTypes.bool,

  /** Define the size of the Tag */
  size: PropTypes.oneOf(["small", "large"]),

  /** If TRUE change the  background color to an hover styles,d efault is <b>false</b> */
  isSelected: PropTypes.bool,

  /** If TRUE add box-shadow in style */
  withBoxShadow: PropTypes.bool,

  /** Define the format for tag, default format is <b>cylindrical</b> */
  format: PropTypes.oneOf(["squared", "circled", "cylindrical"]),

  /** If <b>true</b> disable component click and hover */
  disabled: PropTypes.bool,
};

const tagDefaultProps = {
  variant: "contained",
  borderRadius: 10,
  color: null,
  hoverable: false,
  size: "small",
  isSelected: false,
  withBoxShadow: false,
  format: "cylindrical",
  disabled: false,
};

const Tag = ({
  children,
  variant,
  color,
  borderRadius,
  hoverable,
  isSelected,
  size,
  disabled,
  withBoxShadow,
  format,
  ...rest
}) => {
  const isHoverable = () => {
    if (disabled) return false;

    return hoverable;
  };

  return (
    <TagContainer
      isSelected={isSelected}
      size={size}
      variant={disabled ? "opaque" : variant}
      disabled={disabled}
      color={color}
      hoverable={isHoverable()}
      borderRadius={borderRadius}
      withBoxShadow={withBoxShadow}
      format={format}
      {...rest}
    >
      <p>{children}</p>
    </TagContainer>
  );
};

Tag.propTypes = tagPropTypes;
Tag.defaultProps = tagDefaultProps;

export { Tag, tagPropTypes, tagDefaultProps };
