import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Tag, tagPropTypes, tagDefaultProps } from "../../atoms";
import CardLoader from "../cellLoaders/CardLoader";
import { Tags, TrimTag, SpanTags } from "./styles";

const tagGroupPropTypes = {
  /** Define the variant of all Tags */
  variant: tagPropTypes.variant,

  /** Define the border radius of all Tags */
  borderRadius: tagPropTypes.borderRadius,

  /** If this props is TRUE, add in crusor pointer to all tags */
  hoverable: tagPropTypes.hoverable,

  /** Define the size for tags */
  size: tagPropTypes.size,

  /** Array of <b>Tag</b> */
  tags: PropTypes.arrayOf(
    PropTypes.shape({
      variant: PropTypes.oneOf(["live", "contained"]),

      name: PropTypes.string.isRequired,

      /** Define the background tag color */
      color: PropTypes.string,

      /** Define the borderRadius tag color */
      borderRadius: PropTypes.number,
    })
  ),

  /** If TRUE display an max of one <b>Tag</b>, if array has more than one tah,
   * add an <b>Tag</b> with an ellipsis.
   */
  trim: PropTypes.bool,

  /** If TRUE add an flex-wrap in <b>Tags</b> */
  wrap: PropTypes.bool,

  /** If <b>true</b> render an Loader instead of the component, default value is <b>false</b> */
  isLoading: PropTypes.bool,

  /** Define maxWidth <b>Tag</b> */
  tagsMaxWidth: PropTypes.number,
};

const tagGroupDefaultProps = {
  tags: [],
  variant: tagDefaultProps.variant,
  borderRadius: tagDefaultProps.borderRadius,
  trim: false,
  wrap: false,
  isLoading: false,
  size: tagDefaultProps.size,
  hoverable: tagDefaultProps.hoverable,
  tagsMaxWidth: null,
};

/** TagGroup is normaly used to group children components in the broadcast room
 *
 */
function TagGroup({
  tags,
  trim,
  wrap,
  size,
  variant,
  isLoading,
  hoverable,
  borderRadius,
  tagsMaxWidth,
  ...rest
}) {
  if (isLoading)
    return (
      <div style={{ display: "flex" }}>
        <CardLoader style={{ height: 30, width: 70, marginRight: 5 }} />
        <CardLoader style={{ height: 30, width: 70, marginRight: 5 }} />
        <CardLoader style={{ height: 30, width: 70, marginRight: 5 }} />
      </div>
    );

  return (
    <Tags {...rest} wrap={wrap}>
      {tags &&
        tags.map((tag, index) => {
          if (!trim)
            return (
              <Tag
                size={size}
                hoverable={hoverable}
                key={tag.id}
                variant={tag.variant || variant}
                borderRadius={borderRadius}
                style={{ marginBottom: wrap ? 4 : 0, maxWidth: tagsMaxWidth }}
                {...tag}
              >
                {tag.name}
              </Tag>
            );

          if (index === 0)
            return (
              <Tag
                size={size}
                hoverable={hoverable}
                key={tag.id}
                variant={tag.variant || variant}
                borderRadius={borderRadius}
                style={{ maxWidth: tagsMaxWidth }}
                {...tag}
              >
                {tag.name}
              </Tag>
            );

          return null;
        })}

      {trim && tags.length > 1 && (
        <TrimTag borderRadius={borderRadius}>
          ...
          <SpanTags className="span-tags">
            {tags &&
              tags.map(
                (tag, index) =>
                  index > 0 && (
                    <Tag
                      size={size}
                      hoverable={hoverable}
                      borderRadius={borderRadius}
                      className="span-tag"
                      key={tag.id}
                      style={{ maxWidth: tagsMaxWidth }}
                      {...tag}
                    >
                      {tag.name}
                    </Tag>
                  )
              )}
          </SpanTags>
        </TrimTag>
      )}
    </Tags>
  );
}

TagGroup.propTypes = tagGroupPropTypes;

TagGroup.defaultProps = tagGroupDefaultProps;

export { TagGroup, tagGroupPropTypes, tagGroupDefaultProps };
