import React from "react";
import { useSelector } from "react-redux";

//* PROVIDERS
import { BroadcastRoomProvider } from "./hooks";

//* CUSTOM IMPORTS
import BroadcastRoomFragment from "./components/BroadcastFragment";
import { Spinner } from "~/shared/components/atoms";
import { usePip } from "~/modules/signed/hooks/pip";

function Content() {
  const { data: broadcastRoom, isLoaded } = useSelector(
    state => state.rooms.transmissionRoom
  );

  //* FUNCTIONS
  const { closePip } = usePip();

  //* FUNCTIONS
  React.useEffect(() => {
    closePip();
  }, [closePip]);

  return !isLoaded ? (
    <Spinner centered />
  ) : (
    <BroadcastRoomFragment broadcastRoom={broadcastRoom} />
  );
}

const BroadcastRoom = () => (
  <BroadcastRoomProvider>
    <Content />
  </BroadcastRoomProvider>
);

export default BroadcastRoom;
