import api from "~/shared/services/api";

export const getNotificationsResume = ({
  startDate,
  endDate,
  notificationId,
  action,
}) =>
  Promise.resolve(
    api.get("/admin/notifications/logs/export", {
      params: {
        start_date: startDate,
        end_date: endDate,
        notification_id: notificationId,
        action,
      },
    })
  );
