import produce from "immer";
import { isDataValid } from "~/shared/utils/validations";

import authTypes from "~/modules/auth/store/auth/types";

const INITIAL_STATE = {
  profile: {
    data: {},
    isLoading: false,
    registrationCompleted: false,
    isUpdatingAvatar: false,
  },
};

export const getUserLogged = state => state.user.profile.data;

export default function userReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // SIGN IN
      case authTypes.SIGN_IN.SUCCESSFUL: {
        draft.profile.data = action.payload.user;
        draft.profile.registrationCompleted = action.payload.user.completed;
        break;
      }

      // SIGN UP
      case authTypes.SIGN_UP.SUCCESSFUL: {
        const { user } = action.payload;

        draft.profile.data = user;
        break;
      }

      // COMPLETE SIGN UP
      case "@user/COMPLETE_SIGNUP":
      case "@user/UPDATE_PROFILE": {
        draft.profile.isLoading = true;

        break;
      }

      // COMPLETE SIGN UP SUCCESSGUL
      case "@user/COMPLETE_SIGNUP_SUCCESSFUL":
      case "@user/UPDATE_PROFILE_SUCCESSFUL":
      case "@user/UPDATE_AVATAR_SUCCESSFUL": {
        const { user } = action.payload;

        if (isDataValid(user)) {
          draft.profile.data = user;
          draft.profile.registrationCompleted = true;
        }

        draft.profile.isLoading = false;
        draft.profile.isUpdatingAvatar = false;

        break;
      }

      // REFRESH PROFILE
      case "@user/REFRESH_PROFILE_SUCCESSFUL": {
        const { user } = action.payload;

        if (isDataValid(user)) draft.profile.data = user;

        break;
      }

      // UPDATE AVATAR
      case "@user/UPDATE_AVATAR": {
        draft.profile.isUpdatingAvatar = true;

        break;
      }

      // API ERROR
      case "@user/ERROR": {
        draft.profile.isLoading = false;
        draft.profile.isUpdatingAvatar = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.profile.data = {};
        draft.profile.isLoading = false;
        draft.profile.registrationCompleted = false;
        draft.profile.isUpdatingAvatar = false;

        break;
      }

      default:
    }
  });
}
