import React from "react";
import PropTypes from "prop-types";
import { TiPlus, TiMinus } from "react-icons/ti";

//* EMOJIS
import { ReactComponent as LikeEmoji } from "../../../assets/static/likeEmoji.svg";
import HeartEmoji from "../../../assets/static/heartEmoji@2x.png";
import SmileEmoji from "../../../assets/static/smileEmoji@2x.png";
import SurpriseEmoji from "../../../assets/static/surpriseEmoji@2x.png";

//* CUSTOM IMPORTS
import { Container, Emoji, MoreEmojisButton } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const emojiAnimTime = 700;

const reactionsPropTypes = {
  /** Class name, default is reactions */
  className: PropTypes.string,
  /** Callback that is triggered when user click plus button */
  onOpenReactions: PropTypes.func,
  /** Callback that is triggered when user click an reaction */
  onReactionClick: PropTypes.func,
  /** Props to memo the last reaction clicked */
  lastReaction: PropTypes.string,
};

const reactionsDefaultProps = {
  className: "reactions",
  onOpenReactions: () => {},
  onReactionClick: () => {},
  lastReaction: null,
};

/** Reactions is commonly used inside <b>BroadcastRoom</b>, to catch the user
 * reactions for that room.
 */
function Reactions({
  className,
  onOpenReactions,
  onReactionClick,
  lastReaction,
}) {
  //* REFS

  const time = React.useRef(null);

  //* STATES
  const [showMoreEmojis, setMoreEmojis] = React.useState(false);
  const [isEmojisVisible, setIsEmojisVisible] = React.useState(false);
  const [isOpening, setIsOpening] = React.useState(false);
  const [animationArray, setAnimationArray] = React.useState([]);

  //* *Emojis type was getting in HERE https://gist.github.com/rxaviers/7360908 */
  const { translate } = useTranslation();

  const emojiTypes = {
    likeEmoji: {
      name: translate("components.reactions.like"),
      icon: <LikeEmoji />,
      isSvg: true,
      id: ":thumbsup:",
      isVisible: true,
    },
    loveEmoji: {
      name: translate("components.reactions.love"),
      icon: HeartEmoji,
      id: ":sparkling_heart:",
      isVisible: isEmojisVisible,
    },
    hahaEmoji: {
      name: translate("components.reactions.haha"),
      icon: SmileEmoji,
      id: ":joy:",
      isVisible: isEmojisVisible,
    },
    wowEmoji: {
      name: translate("components.reactions.wow"),
      icon: SurpriseEmoji,
      id: ":hushed:",
      isVisible: isEmojisVisible,
    },
  };

  const { likeEmoji, loveEmoji, hahaEmoji, wowEmoji } = emojiTypes;

  const emojis = [likeEmoji, loveEmoji, hahaEmoji, wowEmoji];

  const setEmoji = React.useCallback(
    emojiType => {
      switch (emojiType) {
        case ":thumbsup:":
          return likeEmoji;

        case ":sparkling_heart:":
          return loveEmoji;

        case ":joy:":
          return hahaEmoji;

        case ":hushed:":
          return wowEmoji;

        default:
          return null;
      }
    },
    [hahaEmoji, likeEmoji, loveEmoji, wowEmoji]
  );

  const handleEmojiAnimation = React.useCallback(
    (emoji, isSideEffect = false) => {
      //* If this function is requested by an sideEffect instead of a user
      //* click do not trigger animation if all emojis is not visible */
      if (isSideEffect && !showMoreEmojis) return;

      const { isSvg } = emoji;

      if (isSvg) {
        clearTimeout(time.current);

        setAnimationArray([...animationArray, emoji.id]);

        time.current = setTimeout(() => {
          setAnimationArray([]);
        }, emojiAnimTime);
      } else {
        const emojiContainer = document.getElementById(
          `${className}${emoji.id}`
        );
        //* Create a new img element
        const img = document.createElement("img");
        img.src = emoji.icon;
        //* Add classname to control animation
        img.className = "animated-emoji";
        emojiContainer.appendChild(img);
        //* Destroy element after animation time effect
        setTimeout(() => emojiContainer.removeChild(img), emojiAnimTime);
      }
    },
    [animationArray, className, showMoreEmojis]
  );

  const handleShowMoreEmojis = () => {
    setIsOpening(true);

    setMoreEmojis(!showMoreEmojis);

    if (showMoreEmojis) {
      setIsEmojisVisible(false);
    } else {
      setTimeout(() => setIsEmojisVisible(true), 290);
    }

    setTimeout(() => setIsOpening(false), 290);

    onOpenReactions(!showMoreEmojis);
  };

  const handleEmojiClick = emoji => {
    onReactionClick(emoji.id);

    handleEmojiAnimation(emoji);
  };

  React.useMemo(() => {
    if (lastReaction !== null)
      handleEmojiAnimation(setEmoji(lastReaction.emoji_type), true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lastReaction]);

  return (
    <Container className={className} showMoreEmojis={showMoreEmojis}>
      {emojis.map(emoji => (
        <Emoji
          key={emoji.id}
          title={emoji.name}
          isVisible={emoji.isVisible}
          emojiAnimTime={emojiAnimTime}
        >
          {animationArray.map(
            animationId =>
              animationId === emoji.id && (
                <div key={animationId} className="animated-emoji">
                  {emoji.icon}
                </div>
              )
          )}

          {emoji.isSvg ? (
            <button
              id={`${className}${emoji.id}`}
              type="button"
              onClick={() => handleEmojiClick(emoji)}
            >
              {emoji.icon}
            </button>
          ) : (
            <button
              id={`${className}${emoji.id}`}
              type="button"
              onClick={() => handleEmojiClick(emoji)}
            >
              <img src={emoji.icon} alt={emoji.name} />
            </button>
          )}
        </Emoji>
      ))}

      <MoreEmojisButton
        className="reactions-add-button"
        disabled={isOpening}
        type="button"
        title={showMoreEmojis ? "Esconder Reações" : "Mais Reações"}
        onClick={handleShowMoreEmojis}
      >
        {showMoreEmojis ? <TiMinus /> : <TiPlus />}
      </MoreEmojisButton>
    </Container>
  );
}

Reactions.propTypes = reactionsPropTypes;

Reactions.defaultProps = reactionsDefaultProps;

export { Reactions, reactionsPropTypes, reactionsDefaultProps };
