import styled from "styled-components";

const calculatePadding = (hasCounter, size) => {
  if (!hasCounter) return 0;

  return size * 0.16;
};

const renderColor = (colors, variant) => {
  switch (variant) {
    case "accent":
      return colors.accent;

    case "primary":
      return colors.primary;

    default:
      return "#16D900";
  }
};

export const Container = styled.div`
  border-radius: ${props =>
    props.borderRadius ? `${props.borderRadius}px` : "50%"};
  display: flex;
  align-items: center;
  justify-content: center;
  height: ${props =>
    props.size + calculatePadding(props.counter, props.size)}px;
  width: ${props => props.size + calculatePadding(props.counter, props.size)}px;
  background-color: ${props =>
    props.color || renderColor(props.theme.colors, props.variant)};
  border: ${props =>
    !props.disableOutline &&
    `1px solid
    ${props.outlineColor || props.theme.colors.surface}`};
  box-shadow: ${props =>
    props.withBoxShadow && `0 2px 2px ${props.theme.colors.shadow}`};
  color: ${props => props.theme.colors.surface};
  font-size: ${props => props.size * 0.583}px;
  font-weight: bold;
`;
