import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { ROOM_VIEW_TYPE, convertViewType } from "~/shared/utils/types";
import { Box } from "~/shared/components/atoms";
import { AdminFileInput } from "~/modules/signed/modules/cms/components/AdminFileInput";
import { useTranslation } from "~/shared/hooks/translation";
import { Container, TranslateInput, Dropdown, Groups } from "./styles";

function InformationTab({
  groupIds,
  setGroupIds,
  viewType,
  setViewType,
  banner,
}) {
  const { translate } = useTranslation();

  const viewTypes = [
    { id: 1, name: translate("common.public"), value: ROOM_VIEW_TYPE.PUBLIC },
    { id: 2, name: translate("common.private"), value: ROOM_VIEW_TYPE.PRIVATE },
    { id: 3, name: translate("common.hide"), value: ROOM_VIEW_TYPE.HIDE },
  ];

  return (
    <Container>
      <Box flex={1} zIndex={1}>
        <TranslateInput
          title={translate("entities.certificates.inputTitle.title")}
          name="title"
          placeholder={translate(
            "entities.certificates.inputTitle.placeholder"
          )}
        />

        <TranslateInput
          title={translate("entities.certificates.inputDescription.title")}
          name="description"
          placeholder={translate(
            "entities.certificates.inputDescription.placeholder"
          )}
        />

        <Dropdown
          title={translate("entities.certificates.dropdownAvailability.title")}
          name="view_type"
          options={viewTypes}
          initialValue={convertViewType(viewType)}
          onChange={value => setViewType(value)}
        />

        <Groups
          title={translate("entities.certificates.groups.title")}
          selectedGroups={groupIds}
          setSelectedGroups={setGroupIds}
        />
      </Box>

      <Box flex={1} marginLeft="24px">
        <AdminFileInput
          title={translate("entities.certificates.inputFile.title")}
          name="banner"
          heigth={128}
          width={279}
          initialValue={banner}
        />
      </Box>
    </Container>
  );
}

InformationTab.propTypes = {
  groupIds: PropTypes.arrayOf(PropTypes.object).isRequired,
  setGroupIds: PropTypes.func.isRequired,
  viewType: PropTypes.object.isRequired,
  setViewType: PropTypes.func.isRequired,
  banner: PropTypes.any.isRequired,
};

export default InformationTab;
