import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import MaterialBackdrop from "@material-ui/core/Backdrop";

//* CUSTOM IMPORTS
import { Box } from "../Box";
import { layers } from "../../../styles";

const useStyles = makeStyles(() => ({
  backdrop: {
    zIndex: layers.modal,
  },
}));

const backdropPropTypes = {
  /** Set Backdrop visibility */
  show: PropTypes.any,

  /** Close modal function */
  onClose: PropTypes.func,

  /** Render a description */
  children: PropTypes.node.isRequired,
};

const backgropDefaultProps = {
  show: false,
  onClose: () => {},
};

/** Backdrop is just an div that focus the children element* */
const Backdrop = ({ show, onClose, children, ...rest }) => {
  const classes = useStyles();

  const handleClose = () => {
    onClose();
  };

  return (
    <MaterialBackdrop
      {...rest}
      className={classes.backdrop}
      open={show}
      onClick={handleClose}
    >
      <Box onClick={e => e.stopPropagation()}>{children}</Box>
    </MaterialBackdrop>
  );
};

Backdrop.propTypes = backdropPropTypes;

Backdrop.defaultProps = backgropDefaultProps;

export { Backdrop, backdropPropTypes, backgropDefaultProps };
