import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Button } from "./styles";

const linkButtonPropTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string])
    .isRequired,

  /** Variant define the color of the link, default is "accent" */
  variant: PropTypes.oneOf(["error", "accent", "primary", "opaque"]),
};

const linkButtonDefaultProps = {
  variant: "accent",
};

/** LinkButton is used to mimic an link styles, but act like an normal button */
function LinkButton({ children, variant, ...rest }) {
  return (
    <Button type="button" variant={variant} {...rest}>
      {children}
    </Button>
  );
}

LinkButton.propTypes = linkButtonPropTypes;

LinkButton.defaultProps = linkButtonDefaultProps;

export { LinkButton, linkButtonPropTypes, linkButtonDefaultProps };
