import React, { memo } from "react";
import PropTypes from "prop-types";
import { CellMeasurerCache } from "react-virtualized";

//* CUSTOM IMPORTS
import { useUserChat } from "../../../..";
import { IndividualChat } from "./styles";
import { useChatSocket } from "../../../../../websocket/channels/chat";
import { useSigned } from "~/modules/signed/hooks";
import { Empty } from "~/shared/components/molecules";
import { getFullDate, isSameDay } from "~/shared/utils/dates";
import { Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { pushState } from "~/shared/services/history";

const chatPropTypes = {
  chatId: PropTypes.number.isRequired,
  userId: PropTypes.number.isRequired,
};

const cache = new CellMeasurerCache({
  fixedWidth: true,
  defaultHeight: 33,
  minHeight: 33,
});

const Chat = memo(({ chatId, userId, ...rest }) => {
  const {
    minimizeChat,
    closeChat,
    getChatHistoryData,
    getChatHistory,
    openedsChats,
    focusChatInput,
    addChatHistoryMessages,
  } = useUserChat();
  const { emitSendMessage } = useChatSocket();
  const { isMyUserId } = useSigned();
  const { translate } = useTranslation();

  //* STATES
  const [messages, setMessages] = React.useState({
    data: [],
    isLoaded: false,
  });
  const [isPaging, setIsPaging] = React.useState(false);
  const [scrollToTop, setScrollToTop] = React.useState(false);
  const [replyMessageId, setReplyMessageId] = React.useState(null);

  const isTheFirstOne = openedsChats[0].id === chatId;

  //* FUNCTIONS
  React.useEffect(() => {
    if (chatId) setMessages(getChatHistoryData(chatId));
  }, [chatId, getChatHistoryData]);

  React.useEffect(() => {
    if (isTheFirstOne) focusChatInput(chatId);
  }, [chatId, focusChatInput, isTheFirstOne]);

  const handlePagination = () => {
    const { page, lastPage } = messages;

    if (page < lastPage) {
      setIsPaging(true);

      getChatHistory(page + 1, chatId)
        .then(response => {
          addChatHistoryMessages(chatId, response.data);

          cache.clearAll();

          setScrollToTop(true);

          setIsPaging(false);
        })
        .catch(() => setIsPaging(false));
    }
  };

  const sendMessage = React.useCallback(
    message => {
      setScrollToTop(false);
      setReplyMessageId(null);

      emitSendMessage(message, chatId, replyMessageId);
    },
    [chatId, emitSendMessage, replyMessageId]
  );

  const renderData = () => {
    if (!messages.data.length) return [];

    const markers = [];

    const formattedMessages = messages.data
      .map((message, index) => {
        const {
          user_id: messageUserId,
          created_at,
          id,
          read,
          repliedMessage,
        } = message;

        if (index < messages.data.length - 1) {
          const lastMessage = messages.data[index + 1];

          const content = getFullDate(created_at, true, false, true);

          const newMarker = {
            id,
            content,
          };

          if (
            !isSameDay({
              dateLeft: created_at,
              dateRight: lastMessage.created_at,
            })
          ) {
            markers.push(newMarker);
          }
        }

        const isMyMessage = isMyUserId(messageUserId);

        let iconName = null;

        if (isMyMessage) {
          if (read) {
            iconName = "doubleCheck";
          } else {
            iconName = "check";
          }
        }

        return {
          ...message,
          time: created_at,
          isMyMessage,
          iconName,
          repliedMessage: repliedMessage && repliedMessage.message,
        };
      })
      .reverse();

    markers.forEach(marker => {
      const index = formattedMessages.findIndex(
        message => message.id === marker.id
      );

      formattedMessages[index] = {
        ...formattedMessages[index],
        marker: marker.content,
      };
    });

    return formattedMessages;
  };

  return (
    <IndividualChat
      {...rest}
      onProfileOpen={() => pushState(`/profile/${userId}`)}
      inputId={chatId}
      onReply={replyMessage => {
        setReplyMessageId(replyMessage.id);
        setScrollToTop(false);
      }}
      onReplyClose={() => setReplyMessageId(null)}
      data={renderData()}
      isLoading={!messages.isLoaded}
      firstOne={isTheFirstOne}
      perPage={scrollToTop ? messages.perPage : messages.data.length}
      disableScrollListener={messages.page >= messages.lastPage}
      isPaging={isPaging}
      onTopReached={handlePagination}
      onMinimize={() => minimizeChat(chatId)}
      onClose={() => closeChat(chatId)}
      onSend={sendMessage}
      emptyComponent={
        <Empty
          icon={<Icon iconName="chat" />}
          message={translate("entities.chat.empty.message")}
          description={translate("entities.chat.empty.description")}
          style={{ padding: 20 }}
        />
      }
      virtualizedListCache={cache}
    />
  );
});

Chat.propTypes = chatPropTypes;

export { Chat };
