import api from "~/shared/services/api";

export const listUserMeetings = ({ page, search }) =>
  api.get("/meeting/schedule", { params: { page, search } });

export const showUserMeeting = ({ meetingId }) =>
  api.get(`/meeting/schedule/${meetingId}`);

export const createUserMeeting = ({
  startDate,
  endDate,
  partnerId,
  playerType,
}) =>
  api.post("/meeting/schedule", {
    start_date: startDate,
    end_date: endDate,
    partner_id: partnerId,
    player_type: playerType,
  });

export const deleteUserMeeting = ({ scheduledMeetingId }) =>
  api.delete(`/meeting/schedule/${scheduledMeetingId}`);

//* EVALUATION MEETING
export const showUserMeetingAvarageEvaluation = ({ meetingId }) =>
  api.get("/meeting/evaluation", { params: { meeting_id: meetingId } });

export const createUserMeetingEvaluation = ({
  meetingId,
  evaluation,
  partnerId,
}) =>
  api.post("/meeting/evaluation", {
    meeting_id: meetingId,
    evaluation,
    partner_id: partnerId,
  });
