/* eslint-disable no-unused-vars */
import React from "react";
import Plyr from "plyr";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, Wrapper, Film } from "./styles";
import { hasHTTP } from "../../../utils/validations";

const plyrPlayerPropTypes = {
  /** The video url to be displayed in PLyrPlayer */
  url: PropTypes.string.isRequired,

  /** Define the type of PlyrPlayer, default is "vimeo" */
  type: PropTypes.oneOf(["vimeo", "youtube"]),

  /** An Callback that is triggered when the component is mounting, return the instance of the
   * Plyr to use his functions
   */
  setController: PropTypes.func,

  /** If TRUE, autoplay the video */
  autoPlay: PropTypes.bool,

  /** If has some number, starts the vídeo in this time */
  currentTime: PropTypes.number,

  /** This ID is needed when you have multiple instance of plyr in the same screen,
   * default is "player"
   */
  plyrId: PropTypes.string,

  /** If TRUE, mute the player, default is "false" */
  muted: PropTypes.bool,

  /** By default <b>PlyrPlayer</b> calculate the aspect ratio based on <b>width</b>,
   * so the component needs to know the <b>width</b> to calculate the <b>height</b>, but you can
   * invert this logic by passing "height" on this props, so the component will calculate
   * the <b>width<b/> based on <b>height</b>. Default value is "width"
   */
  calculateBy: PropTypes.oneOf(["height", "width"]),

  className: PropTypes.string,
};

const plyrPlayerDefaultProps = {
  className: "plyr-video-player",
  type: "vimeo",
  autoPlay: false,
  setController: () => {},
  currentTime: 0,
  plyrId: "player",
  muted: false,
  calculateBy: "width",
};

/** An Player Wrapper to remove youtube/vimeo share buttons, and add some custom styles */
function PlyrPlayer({
  url,
  type,
  muted,
  calculateBy,
  autoPlay,
  currentTime,
  setController,
  plyrId,
  ...rest
}) {
  //* FUNCTIONS
  React.useEffect(() => {
    // Create the instance of plyr player
    const player = new Plyr(`#${plyrId}`, {});

    //* Get Plyr controller to be used into another contexts
    setController(player);

    if (player) {
      if (autoPlay) {
        player.on("ready", () => {
          if (currentTime) {
            player.currentTime = currentTime;
            player.muted = true;
            player.muted = muted;
          }

          if (muted) player.muted = true;

          player.play();
        });
      }
    }
  }, [autoPlay, currentTime, muted, plyrId, setController]);

  return (
    hasHTTP(url) && (
      <Wrapper id="plyr-video-wrapper" calculateBy={calculateBy} {...rest}>
        <Film />

        <div id={plyrId} data-plyr-provider={type} data-plyr-embed-id={url} />
      </Wrapper>
    )
  );
}

PlyrPlayer.propTypes = plyrPlayerPropTypes;

PlyrPlayer.defaultProps = plyrPlayerDefaultProps;

export { PlyrPlayer, plyrPlayerPropTypes, plyrPlayerDefaultProps };
