import React from "react";
import PropTypes from "prop-types";
import { MdSend } from "react-icons/md";

// APIs
import {
  listPosterComments,
  createPosterComment,
  deletePosterComment,
} from "~/modules/signed/modules/client/apis/posters/comments";

// CUSTOM IMPORTS
import { useWindow } from "~/shared/hooks/window";
import { isListValid } from "~/shared/utils/validations";
import { deleteItem, unshitItem } from "~/shared/utils/lists";
import { isStringEmpty } from "~/shared/utils/strings";
import { CellList } from "~/shared/components/molecules";
import CommentCell from "./CommentCell";
import CellLoader from "~/shared/components/molecules/cellLoaders/CommentCellLoader";
import {
  Container,
  Title,
  InputFieldset,
  Input,
  SendButton,
  PaginationButton,
} from "./styles";

function CommentList({ className, posterId }) {
  const { isMobile, windowWidth } = useWindow();
  // STATES
  const [comments, setComments] = React.useState({
    total: 0,
    perPage: 0,
    page: 0,
    lastPage: 0,
    data: [],
  });
  const [comment, setComment] = React.useState("");
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isCreating, setIsCreating] = React.useState(false);
  const [isDeleting, setIsDeleting] = React.useState(false);
  const [commentId, setCommentId] = React.useState(false);
  const [isPaging, setIsPaging] = React.useState(false);
  const [hasNextPage, setHasNextPage] = React.useState(true);

  // FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      await listPosterComments({ page: 1, posterId }).then(response => {
        const { data, perPage } = response.data;
        setComments({ ...response.data, data });
        setIsLoaded(true);

        // If response return less itens then itens pagitation then has no more itens to be loaded
        if (response.data.data.length !== perPage) setHasNextPage(false);
      });
    };
    list();
  }, [posterId]);

  async function loadMoreComments() {
    setIsPaging(true);

    await listPosterComments({ page: comments.page + 1, posterId }).then(
      response => {
        const { data, perPage } = response.data;

        if (isListValid(data))
          setComments({ ...response.data, data: [...comments.data, ...data] });

        // If response return less itens then itens pagitation then has no more itens to be loaded
        if (response.data.data.length !== perPage) setHasNextPage(false);
      }
    );

    setIsPaging(false);
  }

  async function sendComment(message) {
    if (isStringEmpty(message)) return;

    // Clear input
    setComment("");
    document.getElementById("comment-input").value = "";

    const commentData = {
      message,
      poster_id: posterId,
    };

    setIsCreating(true);

    await createPosterComment({ commentData }).then(response => {
      const newComment = response.data;

      setComments({
        ...comments,
        data: unshitItem({ list: comments.data, newItem: newComment }),
      });
    });

    setIsCreating(false);
  }

  function handleInputKeyUp(event) {
    if (event.keyCode === 13) {
      if (!event.shiftKey && !isMobile) {
        sendComment(event.target.value);
      }
    }
  }

  async function onCommentDelete(commentId) {
    setCommentId(commentId);

    setIsDeleting(true);

    await deletePosterComment({
      commentId,
    }).then(() => {
      setComments({
        ...comments,
        data: deleteItem({ itemId: commentId, list: comments.data }),
      });
    });

    setIsDeleting(false);
  }

  return (
    <Container className={className}>
      <Title>Comentários</Title>

      <InputFieldset>
        <Input
          id="comment-input"
          type="text"
          placeholder="Deixe aqui seu comentário"
          onKeyUp={handleInputKeyUp}
          value={comment}
          onChange={e => setComment(e.target.value)}
        />
        {!isStringEmpty(comment) && (
          <SendButton
            type="button"
            title="Enviar comentário"
            onClick={() => sendComment(comment)}
          >
            <MdSend />
          </SendButton>
        )}
      </InputFieldset>

      <CellList
        isPaging={isPaging}
        cellId={commentId}
        isDeleting={isDeleting}
        isCreating={isCreating}
        orientation="column"
        isLoaded={isLoaded}
        data={comments.data}
        renderCell={cell => (
          <CommentCell comment={cell} onDelete={onCommentDelete} />
        )}
        renderCellLoader={() => <CellLoader windowWidth={windowWidth} />}
        loaderListSize={6}
        cellStyle={{ marginBottom: 20 }}
        footerComponent={
          hasNextPage && (
            <PaginationButton
              title="Carregar mais comentários"
              type="text"
              onClick={loadMoreComments}
            >
              Ver mais comentários
            </PaginationButton>
          )
        }
      />
    </Container>
  );
}

CommentList.propTypes = {
  className: PropTypes.string,
  posterId: PropTypes.number.isRequired,
};

CommentList.defaultProps = {
  className: null,
};

export default CommentList;
