import React from "react";
import { useParams } from "react-router-dom";

// CUSTOM IMPORTS
import BroadcastRoomForm from "../components/BroadcastRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminBroadcastRooms } from "~/shared/routes/navigation";

import { useTranslation } from "~/shared/hooks/translation";

function CreateMarketRoom() {
  const { translate } = useTranslation();

  const { categoryId } = useParams();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.broadcastRooms.plural"),
        function: openAdminBroadcastRooms,
      },
      {
        id: 2,
        name: translate("entities.broadcastRooms.addRoom"), // Nova sala de transmissão
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return <BroadcastRoomForm categoryId={categoryId} />;
}

export default CreateMarketRoom;
