import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";

//* CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import history from "~/shared/services/history";
import { useTranslation } from "~/shared/hooks/translation";

import {
  createExtraEmbed,
  updateExtraEmbed,
  clearExtraEmbed,
} from "~/modules/signed/modules/cms/modules/admin/store/extraLinks/actions";
import {
  Modal,
  Title,
  Form,
  Input,
  TranslateInput,
  PlayerInput,
  Footer,
} from "./styles";

function ExtraEmbedModal({ extraEmbed }) {
  const { translate } = useTranslation();
  const editMode = isDataValid(extraEmbed);

  //* REDUCER
  const dispatch = useDispatch();

  const { isCreating, isUpdating } = useSelector(
    state => state.adminExtraLinks.extraEmbeds
  );

  //* STATES
  const [show, setShow] = React.useState(true);
  const [height, setHeight] = React.useState(() =>
    editMode ? `${extraEmbed.height}px` : "400px"
  );
  const [formData, setFormData] = React.useState({});

  const extraEmbedId = editMode ? extraEmbed.id : null;

  //* FUNCTIONS
  React.useEffect(() => {
    if (editMode) {
      setFormData(extraEmbed);
    }
  }, [editMode, extraEmbed]);

  React.useEffect(() => {
    return () => {
      dispatch(clearExtraEmbed());
    };
  }, [dispatch]);

  const handleOnClose = () => {
    setShow(false);

    setTimeout(() => history.goBack(), 300);
  };

  const handleSubmit = data => {
    if (!editMode) {
      dispatch(createExtraEmbed(data));
    } else {
      dispatch(updateExtraEmbed(data, extraEmbedId));
    }
  };

  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH") handleOnClose();
  }, [isCreating, isUpdating]);

  return (
    <Modal show={show} onClose={handleOnClose} withScroll>
      <Form
        onSubmit={data => handleSubmit(data)}
        yupValidation={{
          url: Yup.string().required(
            translate("entities.marketRooms.embeds.inputLink.required")
          ),
        }}
        initialData={formData}
      >
        <Title>
          {editMode
            ? `${translate("entities.marketRooms.embeds.edit")}`
            : `${translate("entities.marketRooms.embeds.add")}`}
        </Title>

        <TranslateInput
          name="title"
          title={translate("entities.marketRooms.embeds.inputName.title")}
          placeholder={translate(
            "entities.marketRooms.embeds.inputName.placeholder"
          )}
        />

        <Input
          name="height"
          type="number"
          min="200"
          max="1000"
          defaultValue={editMode ? extraEmbed.height : "400"}
          title={translate("entities.marketRooms.embeds.inputHeight.title")}
          placeholder={translate(
            "entities.marketRooms.embeds.inputHeight.placeholder"
          )}
          onChange={e => setHeight(`${e.target.value}px`)}
        />

        <PlayerInput
          name="url"
          placeholder={translate(
            "entities.marketRooms.embeds.inputLink.placeholder"
          )}
          src={editMode ? extraEmbed.url : null}
          type="vimeo"
          disableVideoTypeValidation
          height={height}
        />

        <Footer
          onCancel={handleOnClose}
          isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
        />
      </Form>
    </Modal>
  );
}

ExtraEmbedModal.propTypes = {
  extraEmbed: PropTypes.object,
};

ExtraEmbedModal.defaultProps = {
  extraEmbed: null,
};

export default ExtraEmbedModal;
