import api from "~/shared/services/api";

export const getUserData = userId => api.get(`/users/profile/${userId}`);

export const getProfileSettings = userId =>
  api.get(`/users/profile/settings/${userId}`);

export const updateProfileSettings = (userId, data) =>
  api.put(`/users/profile/settings/${userId}`, { data });

export const listBookmarks = ({ page, search, profile_id, type }) =>
  api.get(`/bookmarks/${profile_id}/${type}`, {
    params: {
      page,
      search,
      profile_id,
      type,
    },
  });

export const listConnections = ({ page, status, search, profile_id }) =>
  Promise.resolve(
    api.get("/connections", {
      params: {
        page,
        status,
        search,
        profile_id,
      },
    })
  );

export const listUserConnections = ({ page, fetch, search, profile_id }) =>
  Promise.resolve(
    api.get("/connections/user", {
      params: {
        page,
        fetch,
        search,
        profile_id,
      },
    })
  );

export const createConnection = user_connection_id =>
  Promise.resolve(api.post("/connections", user_connection_id));

export const updateConnection = (user_connection_id, status) =>
  Promise.resolve(api.put("/connections", user_connection_id, status));

export const getSettings = userId => api.get(`/users/settings/${userId}`);

export const updateSettings = (userId, data) =>
  api.put(`/users/settings/${userId}`, data);
