import produce from "immer";
import types from "./types";

const INITIAL_STATE = {
  token: null,
  email: null,
  signed: false,

  // LOADING STATES
  isSigningIn: false,
  isSigningUp: false,
  isRecovering: false,
  isReseting: false,
};

export const getToken = state => state.auth.token;
export const getEmail = state => state.auth.email;

export default function auth(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // SIGN IN
      case types.SIGN_IN.REQUEST: {
        draft.isSigningIn = true;
        break;
      }

      case types.SIGN_IN.SUCCESSFUL: {
        draft.token = action.payload.token;
        draft.signed = true;
        draft.email = action.payload.email;
        draft.isSigningIn = false;
        break;
      }
      case types.SIGN_IN.FAILED: {
        draft.isSigningIn = false;
        break;
      }

      // SIGN UP
      case types.SIGN_UP.SUCCESSFUL: {
        const { user, token } = action.payload;

        draft.token = token;
        draft.email = user.email;
        draft.signed = true;
        draft.isSigningUp = false;
        break;
      }
      case types.SIGN_UP.REQUEST: {
        draft.isSigningUp = true;
        break;
      }
      case types.SIGN_UP.FAILED: {
        draft.isSigningUp = false;
        break;
      }

      // RESET LOADING
      case "@auth_RESET_LOADING": {
        draft.isSigningUp = false;
        draft.isSigningIn = false;
        draft.isRecovering = false;
        draft.isReseting = false;
        break;
      }

      // RECOVER PASSWORDS
      case types.RECOVER_PASSWORD.REQUEST: {
        draft.isRecovering = true;
        break;
      }
      case types.RECOVER_PASSWORD.SUCCESSFUL:
      case types.RECOVER_PASSWORD.FAILED: {
        draft.isRecovering = false;
        break;
      }

      // RESET PASSWORDS
      case types.RESET_PASSWORD.REQUEST: {
        draft.isReseting = true;
        break;
      }
      case types.RESET_PASSWORD.SUCCESSFUL:
      case types.RESET_PASSWORD.FAILED: {
        draft.isReseting = false;
        break;
      }

      case "@user/COMPLETE_SIGNUP_SUCCESSFUL":
      case "@user/UPDATE_PROFILE_SUCCESSFUL": {
        const { user } = action.payload;

        draft.email = user.email;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.token = null;
        draft.signed = false;
        draft.email = null;
        draft.isSigningIn = false;
        draft.isSigningUp = false;
        draft.isRecovering = false;
        draft.isReseting = false;

        localStorage.clear();
        break;
      }
      default:
    }
  });
}
