import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Button,
  IconButton,
  iconButtonPropTypes,
  iconButtonDefaultProps,
} from "../../atoms";
import {
  Container,
  Menu,
  MenuItemName,
  MenuItemIcon,
  MenuItem,
} from "./styles";

const simpleMenuPropTypes = {
  /** buttonIcon use an <b>Icon Button</b> as the anchor for <b>Simple </b> */
  buttonIcon: PropTypes.node,

  /** Define the color for the <b>buttonIcon</b> */
  buttonIconColor: iconButtonPropTypes.color,

  /** button use an <b>Button</b> as the anchor for <b>Simple Menu</b> */
  button: PropTypes.node,

  /** An callback to render an List of items inside the <b>Simple Menu</b>
   * the return needs to be an array of object:
   * { id: 1, name: 'name to be displayed', function: callback when user clicks the item, color: 'define the color of the item'}
   */
  fillItems: PropTypes.func,

  /** Callback that is triggered when user open the <b>Simple Menu></b> */
  onOpen: PropTypes.func,

  /** Callback that is triggered when user closes the <b>Simple Menu</b> */
  onClose: PropTypes.func,

  /** if TRUE, all the items be in uppercase */
  uppercaseItems: PropTypes.bool,

  /** Render an children component inside the <b>Simple Menu</b> instead of the list */
  children: PropTypes.element,

  /** Define the position that <b>Simple Menu</b> will be rendered based on his anchor,
   * de default position is "bottom"
   */
  position: PropTypes.oneOf(["top", "left", "bottom", "right"]),

  /** Define the aligment that <b>Simple Menu</b> will be rendered based on his anchor,
   * de default position is "right"
   */
  alignment: PropTypes.oneOf(["top", "left", "bottom", "right"]),

  /** Control menu styles */
  menuStyle: PropTypes.object,

  /** If <b>true</b>, you need to pass an <b>show</b> props to open the menu */
  controlled: PropTypes.bool,

  /** Only works if <b>controlled</b> is <b>true</b>, control the visibility of the
   * menu
   */
  show: PropTypes.bool,

  onItemClick: PropTypes.func,

  buttonIconSize: iconButtonPropTypes.size,
};

const simpleMenuDefaultProps = {
  buttonIcon: null,
  button: null,
  buttonIconColor: iconButtonDefaultProps,
  onClose: () => {},
  onItemClick: () => {},
  fillItems: () => {},
  uppercaseItems: false,
  onOpen: () => {},
  children: null,
  position: "bottom",
  alignment: "right",
  menuStyle: null,
  controlled: false,
  show: false,
  buttonIconSize: iconButtonDefaultProps.size,
};

/** Simple menu is commonly used when you need to focus on a small card with content */
function SimpleMenu({
  buttonIcon,
  buttonIconSize,
  buttonIconColor,
  button,
  fillItems,
  onClose,
  onOpen,
  uppercaseItems,
  children,
  position,
  alignment,
  controlled,
  show,
  onItemClick,
  ...rest
}) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [items, setItems] = React.useState([]);

  const handleClick = event => {
    event.stopPropagation();

    setAnchorEl(event.currentTarget);
    setItems(fillItems());

    onOpen();
  };

  const handleClose = event => {
    event.stopPropagation();

    setAnchorEl(null);
    onClose();
  };

  const renderMenuItens = () =>
    items.length > 0 &&
    items.map(item => (
      <MenuItem
        key={item.id}
        onClick={e => {
          e.stopPropagation();
          onItemClick(item);
          if (item.function) item.function();
          handleClose(e);
        }}
        withLine={item.withLine}
        color={item.color}
      >
        {item.icon && (
          <MenuItemIcon color={item.color}>{item.icon}</MenuItemIcon>
        )}

        <MenuItemName uppercaseItems={uppercaseItems} color={item.color}>
          {item.name}
        </MenuItemName>
      </MenuItem>
    ));

  const renderPosition = () => {
    switch (position) {
      case "left":
        return {
          vertical: "center",
          horizontal: "left",
        };

      default:
        return {
          vertical: "bottom",
          horizontal: "right",
        };
    }
  };

  const renderAligment = () => {
    switch (alignment) {
      case "center":
        return {
          vertical: "center",
          horizontal: "right",
        };

      default:
        return {
          vertical: "top",
          horizontal: "right",
        };
    }
  };

  const showMenu = () => {
    if (controlled)
      if (show) {
        return Boolean(anchorEl);
      } else {
        return null;
      }

    return Boolean(anchorEl);
  };

  return (
    <Container {...rest}>
      {buttonIcon && (
        <IconButton
          type="button"
          aria-controls="simple-menu"
          aria-haspopup="true"
          onClick={handleClick}
          color={buttonIconColor}
          size={buttonIconSize}
        >
          {buttonIcon}
        </IconButton>
      )}

      {button && (
        <Button
          variant="text"
          aria-controls="simple-menu"
          aria-haspopup="true"
          onClick={handleClick}
        >
          {button}
        </Button>
      )}

      <Menu
        id="simple-menu"
        anchorOrigin={renderPosition()}
        transformOrigin={renderAligment()}
        anchorEl={anchorEl}
        keepMounted
        open={showMenu()}
        onClose={handleClose}
        elevation={0}
        getContentAnchorEl={null}
      >
        {children || renderMenuItens()}
      </Menu>
    </Container>
  );
}

SimpleMenu.propTypes = simpleMenuPropTypes;

SimpleMenu.defaultProps = simpleMenuDefaultProps;

export { SimpleMenu, simpleMenuPropTypes, simpleMenuDefaultProps };
