import api from "~/shared/services/api";

export const getAccessByMarketRoom = ({ startDate, endDate, timezone }) =>
  Promise.resolve(
    api.get("admin/users/markets/export", {
      params: {
        start_date: startDate,
        end_date: endDate,
        timezone,
      },
    })
  );

export const getMarketRoomsResume = ({ startDate, endDate, timezone }) =>
  Promise.resolve(
    api.get("admin/markets/export", {
      params: {
        start_date: startDate,
        end_date: endDate,
        timezone,
      },
    })
  );
