import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Line, Typography } from "~/shared/components/atoms";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { SwipeTabs } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

// TAB FRAGMENTS
import AllConnectionsTab from "./components/AllConnectionsTab";
import CommonConnectionsTab from "./components/ConnectionRequestTab";
import ConnectionsRequestTab from "./components/CommonConnectionTab";

const TABS = {
  ALL: 0,
  OTHER: 1,
};

const ConnectionsTab = ({ itsMe, callIf }) => {
  const { setSearchString } = useProfile();
  const { translate } = useTranslation();
  const [tabIndex, setTabIndex] = React.useState(TABS.ALL);

  // TABS
  const tabItens = [
    {
      id: 1,
      name: (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.connection.all")}
        </Typography>
      ),
    },

    {
      id: 2,
      name: itsMe ? (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.connection.request")}
        </Typography>
      ) : (
        <Typography className="bold uppercase">
          {translate("entities.profile.body.connection.common")}
        </Typography>
      ),
    },
  ];

  const handleOnTabChange = index => {
    setSearchString("");
    setTabIndex(index);
  };

  return (
    <>
      {callIf && (
        <>
          <Line style={{ marginTop: 16, marginBottom: 16 }} />
          <Typography className="title accent" style={{ marginBottom: 16 }}>
            {translate("entities.profile.body.connection.plural")}
          </Typography>
          <SwipeTabs
            variant="scrollable"
            controlled
            index={tabIndex}
            onChange={handleOnTabChange}
            size="small"
            alignment="left"
            tabs={tabItens}
          >
            <AllConnectionsTab
              itsMe={itsMe}
              callConnected={tabIndex === TABS.ALL && itsMe}
              callOther={tabIndex === TABS.ALL && !itsMe}
            />
            {itsMe ? (
              <CommonConnectionsTab callConnections={tabIndex === TABS.OTHER} />
            ) : (
              <ConnectionsRequestTab
                callConnections={tabIndex === TABS.OTHER}
              />
            )}
          </SwipeTabs>
        </>
      )}
    </>
  );
};

ConnectionsTab.propTypes = {
  itsMe: PropTypes.bool.isRequired,
  /** If <b>true</b> this component will call the API */
  callIf: PropTypes.bool.isRequired,
};

export { ConnectionsTab };
