import React from "react";
import { PropTypes } from "prop-types";

//* CUSTOM IMPORTS
import { GamificationInfos } from "~/modules/signed/components";
import { API_STATUS } from "~/shared/utils/apis";
import { useProfile } from "../../../../hooks";
import { useProfileGamification } from "../../../../hooks/gamification";
import { Typography } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { ExtractPointsLog } from "./styles";
import { useGamification } from "~/modules/signed/hooks/gamification";

function GamificationTab({ callIf }) {
  const {
    pointsExtract,
    getUserPointsExtract,
    paginateUserPointsExtract,
    pointsExtractApiStatus,
  } = useProfileGamification();
  const { userPoints } = useGamification();
  const { selectedUser } = useProfile();
  const { translate } = useTranslation();

  //* FUNCTIONS

  React.useEffect(() => {
    if (selectedUser && callIf) getUserPointsExtract(1, selectedUser.id);
  }, [callIf, getUserPointsExtract, selectedUser]);

  return (
    <div>
      <Typography className="subTitle bold" style={{ marginBottom: 16 }}>
        {translate("entities.gamification.myPositionInRank")}
      </Typography>

      <GamificationInfos
        position={userPoints && userPoints.rank_position}
        points={userPoints && userPoints.points}
        userName={userPoints && userPoints.name}
        avatar={{ src: userPoints && userPoints.avatar, size: 68 }}
        isLoading={!userPoints}
      />

      <Typography className="subTitle bold" style={{ marginBottom: 16 }}>
        {translate("entities.gamification.myPointsExtract")}
      </Typography>

      <ExtractPointsLog
        pointsLog={pointsExtract}
        isPaging={pointsExtractApiStatus === API_STATUS.PAGING}
        maxHeight="70vh"
        onPaginate={() => {
          if (selectedUser)
            paginateUserPointsExtract(pointsExtract.page + 1, selectedUser.id);
        }}
      />
    </div>
  );
}

GamificationTab.propTypes = {
  /** If <b>true</b> this component will call the API */
  callIf: PropTypes.bool.isRequired,
};

export { GamificationTab };
