import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { useParams } from "react-router-dom";

import SegmentationTab from "./components/SegmentationTab";
import InformationTab from "./components/InformationTab";
import PerformanceTab from "./components/PerformanceTab";

import { openAdminDownloadMaterials } from "~/shared/routes/navigation";
import { uploadFile } from "~/modules/signed/modules/cms/apis/files";
import { useDownloadMaterial } from "../../hooks";
import BannerMusic from "~/shared/assets/static/bannerMusic2x.png";
import BannerVideo from "~/shared/assets/static/bannerVideo2x.png";
import BannerDoc from "~/shared/assets/static/bannerDoc2x.png";
import { Dropzone, SwipeTabs } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import {
  Container,
  Form,
  ContainerDropzone,
  Preview,
  Banner,
  BannerDetails,
  BannerInfo,
  Footer,
} from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function DownloadMaterialForm({ material, editMode, folderId }) {
  const { createMaterial, editMaterial, isLoading } = useDownloadMaterial();
  const { materialEditId } = useParams();
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  //* STATES
  const [selectedGroups, setSelectedGroups] = React.useState([]);
  const [isUploading, setIsUploading] = React.useState(false);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [tabIndex, setTabIndex] = React.useState(0);
  const [fileName, setFileName] = React.useState();
  const [fileType, setFileType] = React.useState();
  const [fileSize, setFileSize] = React.useState();
  const [fileUrl, setFileUrl] = React.useState();
  const [thumbnailUrl, setThumbnailUrl] = React.useState();
  const [formInitialData, setFormInitialData] = React.useState({});
  const [formData, setFormData] = React.useState({});
  const [showModal, setShowModal] = React.useState(false);
  const [viewType, setViewType] = React.useState("public");
  const [error, setError] = React.useState(null);

  //* TABS
  const tabs = [
    {
      id: 1,
      name: translate("common.performance"),
      hidden: !editMode,
    },
    {
      id: 2,
      name: translate("common.information"),
    },
    { id: 3, name: translate("common.segmentation") },
  ];

  const handleChangeBanner = () => {
    if (fileType === "doc" || fileType === "pdf" || fileType === "txt")
      return BannerDoc;
    if (fileType === "mp4" || fileType === "avi" || fileType === "wmv")
      return BannerVideo;
    if (fileType === "mp3" || fileType === "mpeg") return BannerMusic;

    return fileUrl;
  };

  const handleThumbnail = () => {
    if (fileType === "jpeg" || fileType === "png" || fileType === "jpg") {
      return fileUrl;
    }
    return thumbnailUrl;
  };

  React.useEffect(() => {
    if (material) {
      const {
        title,
        thumbnail,
        view_type,
        file_type,
        file_size,
        file,
        file_name,
        group_ids: groupIds,
      } = material;

      setFormInitialData({
        name: title,
      });

      setSelectedGroups(groupIds);
      setFileName(file_name);
      setFileType(file_type);
      setFileSize(file_size);
      setFileUrl(file);
      setThumbnailUrl(thumbnail);
      setViewType(view_type);
      setIsLoaded(true);
    } else {
      setTabIndex(1);
    }
  }, [material]);

  async function handleSaveMaterial() {
    const materialForm = {
      title: formData && formData.name,
      file: fileUrl,
      thumbnail: handleThumbnail(),
      file_name: fileName,
      file_type: fileType,
      type: "file",
      view_type: viewType,
      folder_id: folderId,
      file_size: fileSize,
      group_ids: selectedGroups,
    };

    if (editMode) {
      await editMaterial(materialForm, materialEditId);
    } else if (fileUrl) {
      await createMaterial(materialForm);
    } else {
      setError(translate("common.selectedAMaterial"));
    }

    setShowModal(false);
  }

  async function handleOnChange(file) {
    const data = new FormData();

    data.append("file", file[0]);

    setIsUploading(true);

    try {
      await uploadFile({ file: data }).then(response => {
        setFileName(response.data.file_name);
        setFileType(response.data.file_type);
        setFileSize(response.data.file_size);
        setFileUrl(response.data.url);
      });

      setIsLoaded(true);
      setIsUploading(false);
      setError(false);
    } catch (e) {
      setError(translate("common.erroToSendAMaterial"));
    }
  }

  async function handleOnFileUpload(file) {
    await uploadFile({ file }).then(response => {
      setThumbnailUrl(response.data.url);
    });
  }

  return (
    <Container>
      <ContainerDropzone>
        <Dropzone
          variant={!!error && "error"}
          errorMessage={error}
          style={{ padding: "110px 105px" }}
          placeholder={translate(
            "entities.downloadMaterials.downloadMaterialForm.dropzone.placeholder"
          )}
          acceptedFormats=".pdf, .jpeg, .mp4, .jpg, .doc, .txt, .png, .mp3, .avi, .wmv"
          maxFiles={1}
          onDropAccepted={file => handleOnChange(file)}
          isLoading={isUploading}
        />
        {isLoaded && (
          <>
            <h4>
              {translate(
                "entities.downloadMaterials.downloadMaterialForm.preview.title"
              )}
            </h4>
            <Preview>
              <Banner src={handleChangeBanner} alt="preview" />
              <BannerDetails>
                <BannerInfo>
                  <h4>
                    {translate(
                      "entities.downloadMaterials.downloadMaterialForm.preview.nameMaterial"
                    )}
                  </h4>
                  <span>{fileName}</span>
                </BannerInfo>
                <BannerInfo>
                  <h4>
                    {translate(
                      "entities.downloadMaterials.downloadMaterialForm.preview.typeMaterial"
                    )}
                  </h4>
                  <span>{fileType}</span>
                </BannerInfo>
                <BannerInfo>
                  <h4>
                    {translate(
                      "entities.downloadMaterials.downloadMaterialForm.preview.sizeMaterial"
                    )}
                  </h4>
                  <span>{fileSize}</span>
                </BannerInfo>
              </BannerDetails>
            </Preview>
          </>
        )}
      </ContainerDropzone>
      <Form
        id="download-material"
        initialData={formInitialData}
        onSubmit={data => {
          setFormData(data);
          setShowModal(true);
        }}
        yupValidation={{
          name: Yup.string().required(
            translate(
              "entities.downloadMaterials.downloadMaterialForm.yupValidation"
            )
          ),
        }}
      >
        <SwipeTabs
          size="small"
          alignment="left"
          tabs={tabs}
          controlled
          index={tabIndex}
          onChange={index => setTabIndex(index)}
        >
          {material && <PerformanceTab material={material} />}
          <InformationTab
            fileUrl={thumbnailUrl}
            onFileUpload={handleOnFileUpload}
          />
          <SegmentationTab
            selectedGroups={selectedGroups}
            setSelectedGroups={setSelectedGroups}
            setViewType={setViewType}
            viewType={viewType}
          />
        </SwipeTabs>
      </Form>

      <Footer
        type="submit"
        formId="download-material"
        isLoading={isLoading}
        showModal={showModal}
        onCancel={openAdminDownloadMaterials}
        entity={translate(
          "entities.downloadMaterials.downloadMaterialForm.material"
        )}
        entityName={formData && dTranslate(formData.name)}
        modalType={editMode ? "update" : "create"}
        onConfirm={handleSaveMaterial}
      />
    </Container>
  );
}

DownloadMaterialForm.propTypes = {
  material: PropTypes.object,
  editMode: PropTypes.bool,
  folderId: PropTypes.number,
};

DownloadMaterialForm.defaultProps = {
  material: null,
  editMode: false,
  folderId: null,
};

export default DownloadMaterialForm;
