import React, { createContext, useContext, Fragment } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { ThemeContext } from "styled-components";
import { useLocation } from "react-router-dom";

//* ACTIONS
import { clearCategories } from "../store/broadcastRooms/actions";

//* PROVIDER
import { UserChatSocketProvider } from "./websocket/channels/chat";
import { UserChatProvider } from "./chat";

//* CUSTOM IMPORTS
import { Spinner } from "~/shared/components/atoms";
import { WhatsAppButton } from "~/shared/components/molecules";
import { ClientHeader } from "./components/ClientHeader";
import { useApp } from "~/shared/hooks";
import { UserTemplate } from "~/shared/components/templates";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const UserContext = createContext(null);

const CLIENT_HEADER_VARIANTS = {
  DEFAULT: "default",
  RETURN: "return",
};

export const userProviderPropTypes = {
  children: PropTypes.any.isRequired,
  title: PropTypes.string,
  disableTemplate: PropTypes.bool,
  disableWhatsappButton: PropTypes.bool,
};

export const userProviderDefaultProps = {
  title: null,
  disableWhatsappButton: false,
  disableTemplate: false,
};

const UserProvider = ({
  children,
  title,
  disableTemplate,
  disableWhatsappButton,
  ...rest
}) => {
  const location = useLocation();
  const dispatch = useDispatch();

  //* STATES

  const [isLoading, setIsLoading] = React.useState(false);
  const { whatsAppButton } = useApp();
  const [headerTitle, setTitle] = React.useState(null);

  //* FUNCTIONS
  const { colors } = React.useContext(ThemeContext);
  const { isChangingLang } = useDynamicTranslation();

  React.useMemo(() => {
    setTitle(title);
  }, [title]);

  const setHeaderTitle = React.useCallback(newTitle => {
    setTitle(newTitle);
  }, []);

  const Template = disableTemplate ? Fragment : UserTemplate;

  React.useMemo(() => {
    setIsLoading(isChangingLang);

    if (isChangingLang) dispatch(clearCategories());
  }, [dispatch, isChangingLang]);

  React.useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, [location.pathname]);

  return (
    <UserContext.Provider value={{ headerTitle, setHeaderTitle }}>
      <UserChatSocketProvider>
        <UserChatProvider>
          <>
            <ClientHeader {...rest} headerTitle={headerTitle} />

            <Template>
              {isLoading ? (
                <Spinner
                  size="large"
                  centered
                  style={{
                    position: "relative",
                    top: 80,
                    backgroundColor: colors.background,
                  }}
                />
              ) : (
                children
              )}
            </Template>

            {whatsAppButton && !disableWhatsappButton && (
              <WhatsAppButton link={whatsAppButton} />
            )}
          </>
        </UserChatProvider>
      </UserChatSocketProvider>
    </UserContext.Provider>
  );
};

UserProvider.propTypes = userProviderPropTypes;

UserProvider.defaultProps = userProviderDefaultProps;

function useUser() {
  const context = useContext(UserContext);

  if (!context) throw new Error("useUser must be used within UserProvider");

  return context;
}

export { UserProvider, useUser, CLIENT_HEADER_VARIANTS };
