import React from "react";
import { useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

// ACTIONS
import { showNetworkRooms } from "~/modules/signed/modules/cms/modules/admin/store/networkRooms/actions";

// CUSTOM IMPORTS
import NetworkRoomForm from "~/modules/signed/modules/cms/modules/admin/pages/NetworkRooms/pages/components/NetworkRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminNetworkRooms } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { Spinner } from "~/shared/components/atoms";

import { useTranslation } from "~/shared/hooks/translation";

function EditNetworkRoom() {
  const { translate } = useTranslation();

  // PARAMS
  const { networkRoomId } = useParams();

  // REDUCER
  const dispatch = useDispatch();
  const { data: networkRoom, isLoaded } = useSelector(
    state => state.adminNetworkRooms.object
  );

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.networkRooms.plural"),
        function: openAdminNetworkRooms,
      },
      {
        id: 2,
        name: translate("entities.networkRooms.editNetworking"),
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(networkRoomId))
      dispatch(showNetworkRooms(networkRoomId));

    return () => {
      isMounted = false;
    };
  }, [dispatch, networkRoomId]);

  return isLoaded ? (
    <NetworkRoomForm networkRoom={networkRoom} />
  ) : (
    <Spinner centered />
  );
}

export default EditNetworkRoom;
