import * as React from "react";

const REQUEST_STATUS = {
  IDLE: "idle",
  PENDING: "pending",
  RESOLVED: "resolved",
  REJECTED: "rejected",
};

const asyncReducer = (state, action) => {
  switch (action.type) {
    case REQUEST_STATUS.PENDING: {
      return { status: REQUEST_STATUS.PENDING, data: null, error: null };
    }
    case REQUEST_STATUS.RESOLVED: {
      return {
        status: REQUEST_STATUS.RESOLVED,
        data: action.data,
        error: null,
      };
    }
    case REQUEST_STATUS.REJECTED: {
      return {
        status: REQUEST_STATUS.REJECTED,
        data: null,
        error: action.error,
      };
    }
    default: {
      throw new Error(`Unhandled action type: ${action.type}`);
    }
  }
};

const useAsync = initialState => {
  const [state, dispatch] = React.useReducer(asyncReducer, {
    status: REQUEST_STATUS.IDLE,
    data: null,
    error: null,
    ...initialState,
  });

  const { data, error, status } = state;

  const run = React.useCallback(
    promise => {
      dispatch({ type: REQUEST_STATUS.PENDING });
      promise.then(
        d => {
          dispatch({ type: REQUEST_STATUS.RESOLVED, data: d.data });
        },
        err => {
          dispatch({ type: REQUEST_STATUS.REJECTED, err });
        }
      );
    },
    [dispatch]
  );

  const setData = React.useCallback(
    d => dispatch({ type: REQUEST_STATUS.RESOLVED, d }),
    [dispatch]
  );
  const setError = React.useCallback(
    err => dispatch({ type: REQUEST_STATUS.REJECTED, err }),
    [dispatch]
  );

  return {
    run,
    data,
    error,
    status,
    setData,
    setError,
  };
};

export { useAsync, REQUEST_STATUS };
