export function convertInputType({ typeValue }) {
  switch (typeValue) {
    case INPUT_TYPES.TAGS.value:
      return INPUT_TYPES.TAGS.name;

    case INPUT_TYPES.SELECT.value:
      return INPUT_TYPES.SELECT.name;

    case INPUT_TYPES.TEXT.value:
      return INPUT_TYPES.TEXT.name;

    case INPUT_TYPES.PARAGRAPH.value:
      return INPUT_TYPES.PARAGRAPH.name;

    case INPUT_TYPES.ADRESS.value:
      return INPUT_TYPES.ADRESS.name;

    case INPUT_TYPES.CITY.value:
      return INPUT_TYPES.CITY.name;

    case INPUT_TYPES.STATE.value:
      return INPUT_TYPES.STATE.name;

    case INPUT_TYPES.COUNTRY.value:
      return INPUT_TYPES.COUNTRY.name;

    case INPUT_TYPES.CPF.value:
      return INPUT_TYPES.CPF.name;

    case INPUT_TYPES.CNPJ.value:
      return INPUT_TYPES.CNPJ.name;

    case INPUT_TYPES.DATE.value:
      return INPUT_TYPES.DATE.name;

    case INPUT_TYPES.TIME.value:
      return INPUT_TYPES.TIME.name;

    case INPUT_TYPES.EMAIL.value:
      return INPUT_TYPES.EMAIL.name;

    case INPUT_TYPES.PHONE.value:
      return INPUT_TYPES.PHONE.name;

    case INPUT_TYPES.CEP.value:
      return INPUT_TYPES.CEP.name;

    case INPUT_TYPES.PASSWORD.value:
      return INPUT_TYPES.PASSWORD.name;

    default:
  }

  return null;
}

