export function setInputMask(inputType) {
  switch (inputType) {
    case INPUT_TYPES.CPF.value:
      return "999.999.999-99";

    case "fullCellphone":
      return "+99(99)99999-9999";

    case INPUT_TYPES.PHONE.value:
      return "(99)99999-9999";

    case INPUT_TYPES.CNPJ.value:
      return "99.999.999/9999-99";

    case INPUT_TYPES.CEP.value:
      return "99999-999";

    case INPUT_TYPES.DATE.value:
      return "99/99/9999";

    default:
      return null;
  }
}

export function setInputMode(inputType) {
  switch (inputType) {
    case INPUT_TYPES.CPF.value:
    case INPUT_TYPES.PHONE.value:
    case INPUT_TYPES.CNPJ.value:
    case INPUT_TYPES.CEP.value:
    case INPUT_TYPES.DATE.value:
      return "numeric";

    default:
      return null;
  }
}
