import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { NotificationCard } from "./styles";
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { CONNECTION_TYPES } from "~/shared/utils/types";
import { useTranslation } from "~/shared/hooks/translation";

const connectionNotificationPropTypes = {
  notification: PropTypes.shape({
    description: PropTypes.string,
    title: PropTypes.string,
    user_connection_id: PropTypes.number,
  }).isRequired,
};
const AUTO_CLOSE_INITIAL_VALUE = 20000;

const ConnectionNotification = ({ notification, ...rest }) => {
  const { translate } = useTranslation();
  const { updateConnection } = useProfile();

  return (
    <NotificationCard
      {...rest}
      icon="userAdd"
      variant="popup"
      autoClose={AUTO_CLOSE_INITIAL_VALUE}
      title={notification.title}
      description={notification.description}
      time={translate("common.now")}
      unreadLink={translate("common.clickToAccess")}
      onFooterConfirm={() =>
        updateConnection(
          notification.user_connection_id,
          CONNECTION_TYPES.CONNECTED,
          CONNECTION_TYPES.PENDING
        )
      }
      onFooterCancel={() =>
        updateConnection(
          notification.user_connection_id,
          CONNECTION_TYPES.DISCONNECTED,
          CONNECTION_TYPES.PENDING
        )
      }
      footerButtons={{
        saveButton: translate("entities.profile.body.connection.connect"),
        cancelButton: translate("entities.profile.body.connection.refuse"),
      }}
    />
  );
};

ConnectionNotification.propTypes = connectionNotificationPropTypes;

export { ConnectionNotification };
