import React from "react";
import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";
import { ThemeContext } from "styled-components";

//* NAVIGATIONS
import {
  openProfile,
  openCMSDashboard,
  openHome,
} from "~/shared/routes/navigation";
import { popState, pushState } from "~/shared/services/history";

//* HOOKS
import { useSigned } from "~/modules/signed/hooks";
import { useClientHeader, USER_NAVIGATIONS } from "./hooks/useClientHeader";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

//* STYLES
import { ClientChatButton, ClientNotificationButton } from "./components";
import { Header, Drawer } from "~/shared/components/organisms";
import { Container, HeaderRightButtons } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import { useGamification } from "~/modules/signed/hooks/gamification";

const CLIENT_HEADER_VARIANTS = {
  DEFAULT: "default",
  RETURN: "return",
};

const clientHeaderPropTypes = {
  headerTitle: PropTypes.string,
  /** If this props has some data, and the variant is RETURN,
   * the handleOnLeftButtonClick will return to the this returnPath props
   */
  returnPath: PropTypes.string,
  /** Define the variant for header */
  headerVariant: PropTypes.oneOf([
    CLIENT_HEADER_VARIANTS.DEFAULT,
    CLIENT_HEADER_VARIANTS.RETURN,
  ]),
};

const clientHeaderDefaultProps = {
  headerTitle: null,
  returnPath: null,
  headerVariant: CLIENT_HEADER_VARIANTS.RETURN,
};

/** This component control all ClientHeader data, it implements HEader component and
 * Drawer component.
 */
const ClientHeader = ({ headerTitle, returnPath, headerVariant, ...rest }) => {
  const { header } = React.useContext(ThemeContext);
  const { user, signOut } = useSigned();
  const { pathname } = useLocation();
  const {
    headerNavigation,
    drawerTopNavigation,
    drawerBottomNavigation,
  } = useClientHeader();
  const {
    isLangLoaded,
    changeLanguage,
    currentLanguage,
    fillLanguagesMenu,
  } = useDynamicTranslation();
  const { renderGamificationFrame, userPoints } = useGamification();
  const { translate } = useTranslation();

  //* STATES
  const isReturnVariant = headerVariant === CLIENT_HEADER_VARIANTS.RETURN;
  const [showDrawer, setShowDrawer] = React.useState(false);
  const [selectedNavigation, setSelectedNavigation] = React.useState(null);

  //* FUNCTIONS
  /** This function will check every time URL change, to check whick item
   * all navigation is selected
   */
  React.useEffect(() => {
    /** Concat all navigations */
    const navigations = [
      ...headerNavigation,
      ...drawerTopNavigation,
      ...drawerBottomNavigation,
    ];

    /** Check if navigation pathname is the same as URL */
    const findedNavigation = navigations.find(nav => nav.pathname === pathname);

    /** If find some navigation with the pathname, set this as the new
     * selected navigation */
    if (findedNavigation) setSelectedNavigation(findedNavigation.id);
  }, [drawerBottomNavigation, drawerTopNavigation, headerNavigation, pathname]);

  const handleOnLeftButtonClick = () => {
    /** If Header variant is "return", the left button will return to the
     * returnPath, or to the last page accessed
     */
    if (isReturnVariant) {
      if (!returnPath) openHome();
      else popState();

      return;
    }

    setShowDrawer(state => !state);
  };

  const handleOnHeaderItemClick = item => {
    pushState(item.pathname);
  };

  const handleOnDrawerItemClick = item => {
    setShowDrawer(false);

    if (item.id === USER_NAVIGATIONS.EXIT.id) signOut();

    if (item.id === USER_NAVIGATIONS.DASHBOARD.id)
      openCMSDashboard(user.access_type);

    pushState(item.pathname);
  };

  const handleOnFlagClick = flag => {
    if (flag.id !== currentLanguage.refCode)
      changeLanguage({ ...flag, refCode: flag.id });
  };

  const getUserFirstName = () => user.name.split(" ")[0];

  const myAvatar = React.useMemo(
    () => ({
      src: user.avatar,
      frame: userPoints && renderGamificationFrame(userPoints.rank_position),
    }),
    [renderGamificationFrame, user.avatar, userPoints]
  );

  return (
    <Container>
      <Header
        {...rest}
        title={headerTitle}
        variant={headerVariant}
        onLogoClick={openHome}
        userName={getUserFirstName()}
        onAvatarClick={openProfile}
        rightComponent={
          <HeaderRightButtons>
            <ClientChatButton />
            <ClientNotificationButton />
          </HeaderRightButtons>
        }
        leftButtonTip={
          isReturnVariant
            ? translate("common.return")
            : translate("common.openMenu")
        }
        onItemClick={handleOnHeaderItemClick}
        onLeftButtonClick={handleOnLeftButtonClick}
        leftNavigation={headerNavigation.map(nav => {
          //* If nav is selected add button children and select styles
          if (nav.id === selectedNavigation)
            return { ...nav, children: nav.content, isSelected: true };

          return nav;
        })}
        avatar={myAvatar}
      />

      <Drawer
        variant="primary"
        selectedItem={selectedNavigation}
        show={showDrawer}
        languageMenu={
          isLangLoaded && {
            selectedFlagId: currentLanguage.refCode,
            buttonCountryCode: currentLanguage.countryCode,
            buttonSelectColor: header.text,
            onFlagClick: handleOnFlagClick,
            data: fillLanguagesMenu(),
          }
        }
        userName={user.name}
        userSubTitle={userPoints && `${userPoints.points}pts`}
        onItemClick={handleOnDrawerItemClick}
        onChange={value => setShowDrawer(value)}
        onReturn={() => setShowDrawer(false)}
        topNavigation={[{ id: 1, data: drawerTopNavigation }]}
        bottomNavigation={drawerBottomNavigation}
        avatar={myAvatar}
      />
    </Container>
  );
};

ClientHeader.propTypes = clientHeaderPropTypes;

ClientHeader.defaultProps = clientHeaderDefaultProps;

export { ClientHeader };
