import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography, Icon, Box, iconPropTypes } from "../../../atoms";
import {
  simpleCellPropTypes,
  replyMessageCardPropTypes,
} from "../../../molecules";
import { Card, SimpleMenu, ReplyMessageCard, Message } from "./styles";

const talkMessageCellPropTypes = {
  variant: PropTypes.oneOf(["accent", "default"]),

  message: PropTypes.string.isRequired,

  /** If this props has data, add an <b>SimpleMenu</b> into this compoentn.
   *
   * See all props in <b>Molecules/SimpleMenu</b>
   */
  simpleMenu: PropTypes.shape(simpleCellPropTypes),

  time: PropTypes.string,

  iconName: iconPropTypes.iconName,

  /** Control all props for <b>ReplyMessageCard</b> component. See all props into
   * <b>Molecules/ReplyMessageCard</b>.
   */
  replyMessageCard: PropTypes.shape(replyMessageCardPropTypes),
};

const talkMessageCellDefaultProps = {
  variant: "default",
  simpleMenu: null,
  time: null,
  iconName: null,
  replyMessageCard: null,
};

/** This component is commonly used in Individual Chat to display messages. */
const TalkMessageCell = ({
  variant,
  message,
  time,
  simpleMenu,
  iconName,
  replyMessageCard,
  ...rest
}) => {
  return (
    <Card variant={variant} hasMenu={simpleMenu} {...rest}>
      {replyMessageCard && (
        <ReplyMessageCard
          {...replyMessageCard}
          variant={variant === "accent" && "shadeAccent"}
        />
      )}

      <Box display="flex" position="relative">
        <Message variant={variant}>{message}</Message>

        <Box
          display="flex"
          marginTop="auto"
          marginLeft="auto"
          alignItems="center"
        >
          {time && (
            <Typography className="small opaque talk-message-time">
              {time}
            </Typography>
          )}

          {iconName && (
            <Icon iconName={iconName} size={16} style={{ marginLeft: 4 }} />
          )}
        </Box>

        {simpleMenu && (
          <SimpleMenu
            {...simpleMenu}
            buttonIconSize={10}
            className="talk-message-menu"
            buttonIcon={<Icon iconName="arrowDown" />}
          />
        )}
      </Box>
    </Card>
  );
};

TalkMessageCell.propTypes = talkMessageCellPropTypes;

TalkMessageCell.defaultProps = talkMessageCellDefaultProps;

export {
  TalkMessageCell,
  talkMessageCellPropTypes,
  talkMessageCellDefaultProps,
};
