import React from "react";
import PropTypes from "prop-types";

//* ASSETS
import { ReactComponent as Error404 } from "../../../assets/static/error404.svg";
import { ReactComponent as NotAllowed } from "../../../assets/static/notAllowed.svg";

//* CUSTOM IMPORTS
import { Typography, Button, buttonPropTypes } from "../../atoms";
import { Container } from "./styles";

const notFoundTemplatePropTypes = {
  /** Render an message with an big font size */
  message: PropTypes.string.isRequired,

  /** Render an description with an small font size */
  description: PropTypes.string.isRequired,

  /** Control all button props, see more in <b>Atoms/Button</b> */
  button: PropTypes.shape({ ...buttonPropTypes }),

  /** The variant control the image that will be rendered, default is <b>notFound</b> */
  variant: PropTypes.oneOf(["notAllowed", "notFound"]),
};

const notFoundTemplateDefaultProps = {
  button: null,
  variant: "notFound",
};

/** This template is commonly used when user try to access an page that he cannot */
const NotFoundTemplate = ({ message, description, button, variant }) => (
  <Container>
    {variant === "notAllowed" ? <NotAllowed /> : <Error404 />}

    <Typography className="bigTitle accent centered">{message}</Typography>

    <Typography className="tinyTitle opaque centered" style={{ marginTop: 20 }}>
      {description}
    </Typography>

    {button && (
      <Button {...button} variant="outlined" style={{ marginTop: 30 }} />
    )}
  </Container>
);

NotFoundTemplate.propTypes = notFoundTemplatePropTypes;
NotFoundTemplate.defaultProps = notFoundTemplateDefaultProps;

export { NotFoundTemplate };
