import { takeLatest, put, all } from "redux-saga/effects";

//* APIs
import {
  createNotification,
  deleteNotification,
  updateNotification,
  listNotifications,
} from "../../apis/notifications";

//* CUSTOM IMPORTS
import types from "./types";

import {
  createNotificationFailed,
  createNotificationSuccessful,
  listNotificationsSuccessful,
  deleteNotificationSuccessful,
  deleteNotificationFailed,
  updateNotificationSuccessful,
  updateNotificationFailed,
} from "./actions";

import { validResponseStatus } from "~/shared/utils/apis";

export function* index({ payload }) {
  try {
    const { page } = payload;

    const response = yield listNotifications({ page });

    if (validResponseStatus(response.status))
      yield put(
        listNotificationsSuccessful({
          notifications: response.data.data,
          lastPage: response.data.lastPage,
          page: response.data.page,
        })
      );
  } catch (err) {
    console.log(err);
  }
}

export function* create({ payload }) {
  try {
    const { notification } = payload;

    const { start_repeat: startRepeat, end_repeat: endRepeat } = notification;

    const formattedNotification = {
      ...notification,
      start_repeat: startRepeat,
      end_repeat: endRepeat,
    };

    const response = yield createNotification({ data: formattedNotification });

    if (validResponseStatus(response.status)) {
      const newNotification = response.data;

      yield put(createNotificationSuccessful(newNotification));
    }
  } catch (err) {
    console.log(err);
    yield put(createNotificationFailed());
  }
}

export function* destroy({ payload }) {
  try {
    const { notificationId } = payload;

    const response = yield deleteNotification({ notificationId });

    if (validResponseStatus(response.status)) {
      yield put(deleteNotificationSuccessful(notificationId));
    }
  } catch (err) {
    console.log(err);
    yield put(deleteNotificationFailed());
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { notification, notificationId } = payload;

    const response = yield updateNotification({
      data: notification,
      notificationId,
    });

    if (validResponseStatus(response.status)) {
      const updatedNotification = response.data;

      yield put(updateNotificationSuccessful(updatedNotification));
    }
  } catch (err) {
    yield put(updateNotificationFailed());
    console.log(err);
  }
}

export default all([
  takeLatest(types.listNotifications.REQUEST, index),
  takeLatest(types.createNotification.REQUEST, create),
  takeLatest(types.deleteNotification.REQUEST, destroy),
  takeLatest(types.updateNotification.REQUEST, update),
]);
