// FETCH ROOM SCHEDULE
export function fetchRoomSchedule(roomId) {
  return {
    type: "@schedules/FETCH_ROOM_SCHEDULE",
    payload: { roomId },
  };
}
export function fetchRoomScheduleSuccessful(roomSchedule) {
  return {
    type: "@schedules/FETCH_ROOM_SCHEDULE_SUCCESSFUL",
    payload: { roomSchedule },
  };
}

export function clearRoomSchedule() {
  return {
    type: "@schedules/CLEAR_ROOM_SCHEDULE",
  };
}

export function schedulesAPIError() {
  return {
    type: "@schedules/ERROR",
  };
}
