import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listRegistrationFieldsSuccessful,
  listRegistrationFieldsFailed,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/cadaster_item");

    if (validResponseStatus(response.status)) {
      const registrationFields = response.data;

      yield put(listRegistrationFieldsSuccessful({ registrationFields }));
    }
  } catch (err) {
    yield put(listRegistrationFieldsFailed());
  }
}

export default all([takeLatest(types.LIST.REQUEST, list)]);
