import types from "./types";

// CATEGORIES
export function receiveCategories({ broadcastRoomCategories }) {
  return {
    type: types.CATEGORIES.RECEIVE,
    payload: { broadcastRoomCategories },
  };
}
export function clearCategories() {
  return {
    type: types.CATEGORIES.CLEAR,
  };
}

// BROADCAST ROOMS
export function paginateBroadcastRoom({ broadcastRooms, categoryId }) {
  return {
    type: types.BROADCAST_ROOMS.PAGINATE,
    payload: { broadcastRooms, categoryId },
  };
}

export function receiveLiveBroadcastRooms({ broadcastRooms }) {
  return {
    type: types.BROADCAST_ROOMS.RECEIVE_ON_LIVE,
    payload: { broadcastRooms },
  };
}
