import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { CellList, Empty } from "~/shared/components/molecules";
import { Typography, Box, Line, Card, Icon } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function EmailAudience({ email, ...rest }) {
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  //* FUNCTIONS
  const renderCell = (group, index) => {
    const { title, count } = group;
    return (
      <Box padding="10px">
        <Typography className="bold normal">{dTranslate(title)}</Typography>
        <Typography className="small opaque italic">{`${count}  ${translate(
          "entities.emails.emailDetails.usersCount"
        )}`}</Typography>

        {index < email.groups.length - 1 && <Line style={{ marginTop: 24 }} />}
      </Box>
    );
  };

  return (
    <Box {...rest}>
      <Typography
        className="bold small normal uppercase"
        style={{ marginBottom: 8 }}
      >
        {translate("entities.emails.emailDetails.usersSended")}
      </Typography>
      <Card style={{ width: "100%", padding: 0, maxWidth: 460 }}>
        <CellList
          noScrollReposition
          withScroll
          listStyle={{ maxHeight: 400, padding: "20px 0" }}
          orientation="column"
          isLoaded="true"
          data={email.groups}
          renderCell={renderCell}
          gap={10}
          emptyComponent={
            <Empty
              icon={<Icon iconName="email" />}
              message={translate(
                "entities.emails.emailDetails.sentAll.message"
              )}
              description={translate(
                "entities.emails.emailDetails.sentAll.description"
              )}
              style={{ margin: "0 auto", maxWidth: 300 }}
            />
          }
        />
      </Card>
    </Box>
  );
}

EmailAudience.propTypes = {
  email: PropTypes.object.isRequired,
};

export default EmailAudience;
