import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, IFrame, LiveTag, Thumbnail } from "./styles";
import { PlyrPlayer } from "../../atoms";

const videoPlayerPropTypes = {
  /** The source of video to be rendered */
  src: PropTypes.string.isRequired,

  /** If "youtube" use the <b>PlyrPlayer</b>, if "vimeo" use an <b>IFrame</b> */
  type: PropTypes.oneOf(["youtube", "vimeo"]).isRequired,

  /** If TRUE, render an <b>Tag</b> inside the vídeo */
  onLive: PropTypes.bool,

  /** If TRUE, autoplay the video */
  autoPlay: PropTypes.bool,

  /** Define the time to start the video, works only in <b>PlyrPlayer</b> */
  currentTime: PropTypes.number,

  /** A function to get the <b>PlyrPlayer</b> instance to use his functions  */
  setController: PropTypes.func,

  /** Define the ID of the <b>PlyrPlayer</b> instance, this is used when page has multiple
   * <b>PlyrPlayer</b>  instances
   */
  plyrId: PropTypes.string,

  /** Renders a thumbnail instead of the video, default is 'null' */
  thumbnail: PropTypes.string,

  /** If TRUE change the video to an thumbnail, if this props is TRUE you need to
   * pass the <b>thumbnail</b> props.
   */
  showThumbnail: PropTypes.bool,

  /** If TRUE, mute player, default is "false" */
  muted: PropTypes.bool,
};

const videoPlayerDefaultProps = {
  onLive: false,
  autoPlay: false,
  setController: () => {},
  currentTime: null,
  plyrId: "player",
  thumbnail: null,
  showThumbnail: false,
  muted: false,
};

function VideoPlayer({
  src,
  type,
  plyrId,
  onLive,
  thumbnail,
  showThumbnail,
  autoPlay,
  currentTime,
  setController,
  muted,
  ...rest
}) {
  const trimSrc = String(src).trim();

  return (
    <Container {...rest}>
      {onLive && <LiveTag variant="live">Ao vivo</LiveTag>}

      {showThumbnail ? (
        <Thumbnail src={thumbnail} alt="videoThumbnail" />
      ) : (
        <>
          {type === "vimeo" ? (
            <div id="player-container">
              <IFrame
                id="video-player-iframe"
                allowfullscreen
                webkitallowfullscreen
                mozallowfullscreen
                frameborder="0"
                allow="
                    accelerometer;
                    autoplay
                    encrypted-media;
                    fullscreen;
                    microphone;
                    camera;
                    gyroscope;
                    picture-in-picture;
                    "
                src={`${trimSrc}?muted=${muted ? 1 : 0}&autoplay=${
                  autoPlay ? 1 : 0
                }`}
              />
            </div>
          ) : (
            <PlyrPlayer
              plyrId={plyrId}
              url={trimSrc}
              type={type}
              muted={muted}
              autoPlay={autoPlay}
              currentTime={currentTime}
              setController={setController}
              style={{ width: "100%", height: "100%" }}
            />
          )}
        </>
      )}
    </Container>
  );
}

VideoPlayer.propTypes = videoPlayerPropTypes;

VideoPlayer.defaultProps = videoPlayerDefaultProps;

export { VideoPlayer, videoPlayerPropTypes, videoPlayerDefaultProps };
