import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useImportUsers, TABS_INDEX } from "../../hooks";
import {
  openAdminUserImportSettings,
  openAdminUserImportStatus,
} from "~/shared/routes/navigation";

import { ImportTemplate } from "~/shared/components/templates";
import { Box } from "~/shared/components/atoms";
import { LoadingModal } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { Typography } from "./styles";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const ImportUserReview = ({ onImport }) => {
  const { setFooterButtons } = useCMS();
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();
  const {
    tabIndex,
    dataToBeImported,
    sheetConfig,
    importSheet,
    isImporting,
    clearData,
  } = useImportUsers();

  //* FUNCTIONS
  React.useEffect(() => {
    if (tabIndex === TABS_INDEX.REVIEW)
      setFooterButtons({
        isVisible: true,
        saveButton: translate("common.import"),
        cancelButton: translate("common.return"),
        onClick: () =>
          importSheet(importData => {
            const { id } = importData;

            clearData();
            onImport();
            openAdminUserImportStatus({ importId: id });
          }),
        onCancel: openAdminUserImportSettings,
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tabIndex]);

  if (!sheetConfig) return null;

  const { groups, sendEmail, passwordField } = sheetConfig;

  return (
    <>
      <ImportTemplate title={translate("common.reviewAndImport")}>
        <Box marginTop="40px">
          <Typography>
            <>
              <strong>{dataToBeImported()}</strong>{" "}
              {translate(
                dataToBeImported() > 1
                  ? "entities.users.usersWillBeImported"
                  : "entities.users.userWillBeImported"
              )}
            </>
          </Typography>

          <Box marginLeft="20px">
            {passwordField && (
              <Typography>
                <>
                  <strong>{passwordField.title}</strong>{" "}
                  {translate("common.willBeUsedPasswordFirstAccess")}
                </>
              </Typography>
            )}

            {sendEmail && (
              <Typography>
                <>
                  <strong>{translate("common.anEmailWillBeSent")}</strong>{" "}
                  {translate("common.withTheInstructions")}.
                </>
              </Typography>
            )}

            {groups.length > 0 && (
              <Typography className="bold">{`${translate(
                "common.groups"
              )}: ${groups.map(
                group => `${dTranslate(group.title)} `
              )} `}</Typography>
            )}
          </Box>
        </Box>
      </ImportTemplate>

      <LoadingModal
        show={isImporting === "PENDING"}
        title={translate("common.importingSpreadsheet")}
        description={`${translate(
          "common.importing"
        )} ${dataToBeImported()} ${translate(
          dataToBeImported() > 1
            ? "entities.users.plural"
            : "entities.users.singular"
        )}...`}
        errorMessage={
          isImporting === "FAILED" &&
          `${translate("common.importFailed")}. ${translate(
            "common.tryAgainInAFewMinutes"
          )}`
        }
      />
    </>
  );
};

ImportUserReview.propTypes = {
  onImport: PropTypes.func.isRequired,
};

export default ImportUserReview;
