import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import Loader from "../../molecules/cellLoaders/CheckListCellLoader";
import { getFullHours, getFullDate, isToday } from "../../../utils/dates";
import { Card, Typography, Icon, Box, Spinner } from "../../atoms";
import {
  CellList,
  SearchBar,
  cellListPropTypes,
  searchBarPropTypes,
} from "../../molecules";
import { ChatCell, chatCellPropTypes } from "../cells";
import { useTranslation } from "../../../hooks/translation";
import { pushState } from "../../../services/history";

const chatListPropTypes = {
  data: PropTypes.arrayOf({
    id: PropTypes.number,
    ...chatCellPropTypes,

    /** If <b>true</b> add an silence icon */
    isSilenced: PropTypes.bool,

    /** Control the avatar badge color */
    isOnline: PropTypes.bool,

    /** If <b>true</b> add an block icon */
    isBlocked: PropTypes.bool,
  }).isRequired,

  /** Control the scroll style. */
  scrollStyle: cellListPropTypes.scrollStyle,

  /** Callback that is triggered when user scroll to the bottom of the list. */
  onBottomReached: cellListPropTypes.onBottomReached,

  /** Callback that is triggered when user search an chat. Return the value in <b>SearchBar</b>.
   *
   * Example: (value) => {}
   */
  onSearch: PropTypes.func,

  /** Callback that is triggered when user Clicks into an item of the list.
   *
   * Example: (chat) => {}
   */
  onClick: PropTypes.func,

  /** Callback that is triggered when user Clicks to block an chat.
   *
   * Example: (chat) => {}
   */
  onBlock: PropTypes.func,

  /** Callback that is triggered when user Clicks to silence an chat.
   *
   * Example: (chat) => {}
   */
  onSilence: PropTypes.func,
  /** If <b>true</b>  the list will render loaders */
  isLoading: PropTypes.bool,

  className: PropTypes.string,

  style: PropTypes.object,

  title: PropTypes.string,

  /** If <b>true</b> add an <b>Spinner</b> inside the <b>Searchbar</b> */
  isSearching: PropTypes.bool,

  /** If this props has data, render this element instead of the default <b>ChatList</b> */
  children: PropTypes.element,

  searchDefaultValue: searchBarPropTypes.defaultValue,

  isPaging: PropTypes.bool,

  /** The title for the list of <b>ChatCell</b> */
  listTitle: PropTypes.string,
};

const chatListDefaultProps = {
  scrollStyle: null,
  onBottomReached: () => {},
  onSearch: () => {},
  onClick: () => {},
  onBlock: () => {},
  onSilence: () => {},
  className: "chat-list",
  style: null,
  title: null,
  isLoading: false,
  isSearching: false,
  children: null,
  searchDefaultValue: "",
  isPaging: false,
  listTitle: null,
};

const ChatList = ({
  className,
  style,
  data,
  title,
  listTitle,
  isLoading,
  onClick,
  onSearch,
  searchDefaultValue,
  isSearching,
  scrollStyle,
  onBottomReached,
  // onBlock,
  onSilence,
  children,
  isPaging,
  ...rest
}) => {
  const { translate } = useTranslation();
  const { colors } = React.useContext(ThemeContext);

  //* FUNCTIONS
  const renderIcon = isSilenced => {
    if (isSilenced) return "silenced";

    return null;
  };

  return (
    <Card className={className} style={{ padding: 0, ...style }}>
      <Box padding="16px 16px 0">
        {title && (
          <Typography
            className="accent bold title"
            style={{ marginBottom: 16 }}
          >
            {title}
          </Typography>
        )}

        <SearchBar
          defaultValue={searchDefaultValue}
          style={{ marginBottom: 8 }}
          placeholder={translate("components.chatList.searchBar.placeholder")}
          onChange={onSearch}
          isSearching={isSearching}
        />
      </Box>

      <CellList
        {...rest}
        onBottomReached={onBottomReached}
        scrollStyle={{
          padding: 8,
          maxHeight: "calc(100% - 120px)",
          ...scrollStyle,
        }}
        withScroll
        headerComponent={
          listTitle && (
            <Typography
              className="accent subTitle bold"
              style={{ marginBottom: 8, padding: "0 8px" }}
            >
              {listTitle}
            </Typography>
          )
        }
        noScrollReposition
        orientation="column"
        data={data}
        isLoaded={!isLoading}
        gap={8}
        renderCell={chat => {
          const {
            messageCounter,
            lastMessageTime,
            isSilenced,
            isOnline,
          } = chat;

          return (
            <ChatCell
              {...chat}
              avatarBadgeColor={isOnline ? colors.online : colors.offline}
              variant={messageCounter > 0 && "accent"}
              onClick={() => onClick(chat)}
              lastMessageTime={
                isToday({ date: lastMessageTime })
                  ? getFullHours(lastMessageTime)
                  : getFullDate(lastMessageTime)
              }
              iconName={renderIcon(isSilenced)}
              simpleMenu={{
                fillItems: () => [
                  {
                    id: 1,
                    name: translate("common.accessProfile"),
                    icon: <Icon iconName="person" />,
                    function: () => pushState(`/profile/${chat.user_id}`),
                  },
                  {
                    id: 2,
                    name: translate(
                      isSilenced
                        ? "common.reactivateNotifications"
                        : "common.silence"
                    ),
                    icon: (
                      <Icon
                        iconName={isSilenced ? "notifications" : "silenced"}
                      />
                    ),

                    function: () => onSilence(chat),
                  },
                  /* {
                      id: 2,
                      name: translate(
                        isBlocked ? "common.unlock" : "common.block"
                      ),
                      icon: <Icon iconName={isBlocked ? "block" : "block"} />,
                      color: colors.redWrong,
                      function: () => onBlock(chat),
                    }, */
                ],
              }}
            />
          );
        }}
        renderCellLoader={() => (
          <Loader avatarSize={56} infosX={74} style={{ padding: "0 16px" }} />
        )}
        footerComponent={
          <>
            {isPaging && <Spinner centered />}

            {children}
          </>
        }
        listStyle={6}
      />
    </Card>
  );
};

ChatList.propTypes = chatListPropTypes;

ChatList.defaultProps = chatListDefaultProps;

export { ChatList, chatListPropTypes, chatListDefaultProps };
