import React from "react";
import { useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { showBroadcastRoom } from "~/modules/signed/modules/cms/modules/admin/store/broadcastRooms/actions";
import BroadcastRoomForm from "../components/BroadcastRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminBroadcastRooms } from "~/shared/routes/navigation";
import { Spinner } from "~/shared/components/atoms";

import { useTranslation } from "~/shared/hooks/translation";

function EditBroadcastRoom() {
  const { translate } = useTranslation();

  // PARAMS
  const { broadcastRoomId } = useParams();

  // REDUCER
  const dispatch = useDispatch();
  const { data: broadcastRoom, isLoaded } = useSelector(
    state => state.adminBroadcastRooms.object
  );

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(broadcastRoomId)) {
      dispatch(showBroadcastRoom(broadcastRoomId));
    }

    return () => {
      isMounted = false;
    };
  }, [broadcastRoomId, dispatch]);

  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.broadcastRooms.plural"),
        function: openAdminBroadcastRooms,
      },
      {
        id: 2,
        name: translate("entities.broadcastRooms.streamRoom"),
        function: () => {},
      },
    ]);

    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [setBreadCrumbs, setEditMode, translate]);

  return isLoaded ? (
    <BroadcastRoomForm broadcastRoom={broadcastRoom} />
  ) : (
    <Spinner centered />
  );
}

export default EditBroadcastRoom;
