import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import { addItem, deleteItem, updateItem } from "~/shared/utils/lists";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isDeleting: null,
    isUpdating: null,
    certificateId: null,
  },
  object: {
    data: {},
    isLoaded: false,
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESS: {
        const { certificates } = action.payload;

        if (isDataValid(certificates)) draft.list.data = certificates;

        draft.list.isLoaded = true;

        break;
      }

      // SHOW
      case types.SHOW.SUCCESS: {
        const { certificate } = action.payload;

        if (isDataValid(certificate)) draft.object.data = certificate;

        draft.object.isLoaded = true;

        break;
      }
      case types.SHOW.CLEAR: {
        draft.object.data = {};
        draft.object.isLoaded = false;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";

        break;
      }
      case types.CREATE.SUCCESS: {
        const { certificate } = action.payload;

        draft.list.data = addItem({
          newItem: certificate,
          list: draft.list.data,
        });

        draft.list.isCreating = "FINISH";

        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";

        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        const { certificateId } = action.payload;

        draft.list.certificateId = certificateId;
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESS: {
        const { certificateId } = action.payload;

        draft.list.data = deleteItem({
          itemId: certificateId,
          list: draft.list.data,
        });

        draft.list.isDeleting = "FINISH";
        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";

        break;
      }
      case types.UPDATE.SUCCESS: {
        const { certificate } = action.payload;

        draft.list.data = updateItem({
          updatedItem: certificate,
          list: draft.list.data,
        });

        draft.list.isUpdating = "FINISH";

        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // EVENTS
      case types.EVENTS.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.certificateId = null;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isLoaded = false;
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.certificateId = null;

        draft.object.data = {};
        draft.object.isLoaded = false;
        break;
      }
      default:
    }
  });
}
