import React from "react";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import { setState } from "~/modules/signed/modules/client/store/registrationFields/actions";

// CUSTOM IMPORTS
import { isDataValid } from "~/shared/utils/validations";
import { Dropdown } from "~/shared/components/organisms";
import CityDropdownLoader from "../CitiesDropdown/Loader";

function StatesDropdown({ ...rest }) {
  const dispatch = useDispatch();
  const { attributes } = useSelector(s => s.user.profile.data);

  // STATES
  const [options, setOptions] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(true);

  // FUNCTIONS
  React.useEffect(() => {
    // IBGE STATES
    const listIBGEStates = async () => {
      setIsLoading(true);

      await axios
        .get("https://servicodados.ibge.gov.br/api/v1/localidades/estados")
        .then(response => {
          const formattedOptions = response.data.map(s => {
            return {
              id: s.id,
              name: s.nome,
              value: s.id,
            };
          });

          const sortedOptions = formattedOptions.sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            }
            if (a.name > b.name) {
              return 1;
            }
            return 0;
          });

          setOptions(sortedOptions);
          setIsLoading(false);
        })
        .catch(() => {
          setIsLoading(false);
        });
    };

    listIBGEStates();
  }, [setIsLoading]);

  function handleOnChange(value) {
    dispatch(setState({ stateId: value }));
  }

  return !isLoading ? (
    <Dropdown
      options={options}
      onChange={handleOnChange}
      initialValue={isDataValid(attributes) ? attributes.state : null}
      withSearch
      {...rest}
    />
  ) : (
    <CityDropdownLoader width={328} />
  );
}

export default StatesDropdown;
