import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import {
  Checkbox,
  checkboxPropTypes,
  Box,
  typographyPropTypes,
  Icon,
  Avatar,
  avatarPropTypes,
  avatarDefaultProps,
  Typography,
} from "../../atoms";
import { TagGroup } from "..";
import { stopPropagation } from "../../../utils/events";
import {
  HoverWrapper,
  Container,
  EditButton,
  DeleteButton,
  Buttons,
} from "./styles";

const checkListItemPropTypes = {
  variant: PropTypes.oneOf(["avatared", "tagged", "avataredTagged", "default"]),

  title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]).isRequired,

  /** Define the styles for <b>title</b> */
  titleStyle: PropTypes.shape(typographyPropTypes),

  subTitle: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),

  /** Define the styles for <b>subTitle</b> */
  subTitleStyle: PropTypes.shape(typographyPropTypes),

  /** Control all <b>Checkbox</b> props, see more in <b>Atoms/Checkbox</b>,
   *
   * if this props is "null", remove the <b>Checkbox</b>
   *
   * by default this props come as "null"
   */
  checkbox: PropTypes.shape(checkboxPropTypes),

  /** The URL image for <b>Avatar</b> component.
   *
   * <i>obs: This props only works in <b>avatared</b> variant.</i>
   */
  avatar: PropTypes.string,

  /** Control the size for <b>Avatar</b>  component.
   *
   * <i>obs: This props only works in <b>avatared</b> variant.</i>
   */
  avatarSize: avatarPropTypes.size,

  /** Callback that is triggered when user click into <b>EditButton</b>,
   *
   * if this props is "null", remove the <b>EditButton</b>
   *
   * by default this props come as "null"
   */
  onEdit: PropTypes.func,

  /** Callback that is triggered when user click into <b>DeleteButton</b>,
   *
   * if this props is "null", remove the <b>DeleteButton</b>
   *
   * by default this props come as "null"
   */
  onDelete: PropTypes.func,

  /** An array of <b>Tag</b>, if this props has some data, add an <b>TagGroup</b>
   * component below <b>SubTitle</b>, see all props in <b>Molecules/TagGroup</b>.
   *
   * <i>obs: This props only works in <b>tagged</b> variant.</i>
   */
  tags: PropTypes.arrayOf(
    PropTypes.shape({
      variant: PropTypes.oneOf(["live", "contained"]),

      name: PropTypes.string.isRequired,

      /** Define the background tag color */
      color: PropTypes.string,

      /** Define the borderRadius tag color */
      borderRadius: PropTypes.number,
    })
  ),
};

const checkListItemDefaultProps = {
  titleStyle: null,
  subTitleStyle: null,
  subTitle: null,
  checkbox: null,
  onEdit: null,
  onDelete: null,
  tags: [],
  variant: "default",
  avatar: null,
  avatarSize: avatarDefaultProps.size,
};

function CheckListItem({
  variant,
  title,
  titleStyle,
  subTitle,
  subTitleStyle,
  avatar,
  avatarSize,
  tags,
  checkbox,
  onEdit,
  onDelete,
  ...rest
}) {
  const hasAvatar = variant === "avatared" || variant === "avataredTagged";
  const hasTags = variant === "tagged" || variant === "avataredTagged";

  return (
    <HoverWrapper className="checklist-item" variant="background" {...rest}>
      <Container>
        {checkbox && (
          <Checkbox
            style={{ position: "relative", right: 10 }}
            {...checkbox}
            onClick={e => stopPropagation(e, checkbox.onClick)}
          />
        )}

        <Box
          display="flex"
          width="calc(100% - 110px)"
          style={checkbox && { position: "relative", right: 10 }}
        >
          {hasAvatar && (
            <Avatar src={avatar} size={avatarSize} style={{ marginRight: 8 }} />
          )}

          <Box
            width="100%"
            display="flex"
            flexDirection="column"
            justifyContent="center"
          >
            <Typography maxLines={1} className="normal bold" {...titleStyle}>
              {title}
            </Typography>

            {subTitle && (
              <Typography
                maxLines={2}
                className="small opaque"
                {...subTitleStyle}
              >
                {subTitle}
              </Typography>
            )}

            {hasTags > 0 && (
              <TagGroup tags={tags} trim style={{ marginTop: 4 }} />
            )}
          </Box>
        </Box>
      </Container>

      <Buttons>
        {onEdit && (
          <EditButton
            className="checkboxed-cell-icon-button"
            onClick={e => stopPropagation(e, onEdit)}
          >
            <Icon iconName="edit" />
          </EditButton>
        )}

        {onDelete && (
          <DeleteButton
            className="checkboxed-cell-icon-button"
            onClick={e => stopPropagation(e, onDelete)}
          >
            <Icon iconName="delete" />
          </DeleteButton>
        )}
      </Buttons>
    </HoverWrapper>
  );
}

CheckListItem.propTypes = checkListItemPropTypes;

CheckListItem.defaultProps = checkListItemDefaultProps;

export { CheckListItem, checkListItemPropTypes, checkListItemDefaultProps };
