import React from "react";
import PropTypes from "prop-types";

//* APIs
import { getMarketRoomsResume } from "~/modules/signed/modules/cms/modules/admin/apis/exports/marketRooms";
import { getMarketRoomseportCount } from "~/modules/signed/modules/cms/modules/admin/apis/marketRooms";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

//* CUSTOM IMPORTS
import { DashboardReportCard } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function MarketRoomsCard({ dateInterval, timezone }) {
  //* STATES
  const { status, data: marketRoomsReport, run } = useAsync();
  const isLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    return run(
      getMarketRoomseportCount({
        startDate: dateInterval.startDate,
        endDate: dateInterval.endDate,
      })
    );
  }, [dateInterval.endDate, dateInterval.startDate, run]);

  return (
    <DashboardReportCard
      downloadButtonData={{
        callDownloadSheetsApi: () =>
          getMarketRoomsResume({
            endDate: dateInterval.endDate,
            startDate: dateInterval.startDate,
            timezone,
          }),
        sheetsName: `${translate("entities.reports.singular")} ${translate(
          "entities.marketRooms.singular"
        )}`,
      }}
      isLoaded={isLoaded}
      title={translate("entities.marketRooms.singular")}
      dotColor="#DC2BFD"
      reportItems={[
        {
          id: 1,
          counter: isLoaded && marketRoomsReport.total_access,
          description: translate("entities.marketRooms.standAccesses"),
        },
        {
          id: 2,
          counter: isLoaded && marketRoomsReport.total_unique_access,
          description: translate("common.collectedLeads"),
        },
      ]}
    />
  );
}

MarketRoomsCard.propTypes = {
  dateInterval: PropTypes.object.isRequired,
  timezone: PropTypes.number.isRequired,
};

export default MarketRoomsCard;
