import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  getFullDateBR,
  getCurrentDay,
  getFullHours,
  isToday,
} from "~/shared/utils/dates";
import { getMaterialResume } from "~/modules/signed/modules/cms/modules/admin/apis/exports/downloadMaterials";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminDownloadMaterials } from "~/shared/routes/navigation";
import { AccessDetailsTemplate } from "~/shared/components/templates";
import { Icon, Typography } from "~/shared/components/atoms";
import { BarChart } from "~/shared/components/organisms";
import { useTranslation } from "~/shared/hooks/translation";
import { useDownloadMaterial } from "../../hooks";

function MaterialDetailsFragment({
  materialId,
  title,
  emptyMessage,
  emptyDescription,
  countDescription,
  barChartTipDescription,
}) {
  const {
    cardData,
    tableData,
    pagination,
    getTotalCount,
    getTotalCountAccess,
    isLoaded,
    materialTotalCount,
  } = useDownloadMaterial();

  const { translate } = useTranslation();
  const { setBreadCrumbs } = useCMS();

  //* STATES
  const [dateInterval, setDateInterval] = React.useState({
    startDate: getCurrentDay({ inMidnigth: true, subtractDays: 0 }),
    endDate: new Date(),
  });
  const [search, setSearch] = React.useState("");

  //* FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: translate("entities.downloadMaterials.downloadableMaterials"),
        function: openAdminDownloadMaterials,
      },
      {
        id: 2,
        name: title,
        function: () => {},
      },
    ]);

    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs, title, translate]);

  React.useEffect(() => {
    const { startDate, endDate } = dateInterval;

    getTotalCount({
      startDate,
      endDate,
      materialId,
      granularity:
        isToday({ date: startDate }) && isToday({ date: endDate })
          ? "hour"
          : "day",
    });
  }, [dateInterval, getTotalCount, materialId]);

  const fetchMaterialCountAccess = React.useCallback(
    page => {
      const { startDate, endDate } = dateInterval;

      getTotalCountAccess({
        endDate,
        startDate,
        page,
        search,
        materialId,
      });
    },
    [dateInterval, getTotalCountAccess, materialId, search]
  );

  React.useEffect(() => fetchMaterialCountAccess(1), [
    fetchMaterialCountAccess,
  ]);

  const renderTableRows = () => {
    if (!tableData) return [];

    return tableData.map(row => {
      if (!row) return null;

      const { user, created_at: createdAt } = row;

      return {
        user: user && (
          <Typography className="subTitle bold accent">{user.name}</Typography>
        ),
        email: user && user.email,
        accessIn: `${getFullDateBR(createdAt)} - ${getFullHours(createdAt)}`,
      };
    });
  };

  const isBarLoaded = () => {
    if (cardData) if (cardData.total_count > 0) return true;

    return false;
  };

  return (
    <AccessDetailsTemplate
      onSearch={value => setSearch(value)}
      searchBarPlaceholder={translate("entities.downloadMaterials.search")}
      dateInterval={dateInterval}
      onDateSelect={date => setDateInterval(date)}
      downloadButtonData={{
        placeholder: translate("entities.reports.downloadReportInXlsx"),
        callDownloadSheetsApi: () =>
          getMaterialResume({
            startDate: dateInterval.startDate,
            endDate: dateInterval.endDate,
            materialId,
          }),
        sheetsName: title,
      }}
      cardData={{
        dotColor: "#FD9F2B",
        isLoaded,
        data: [
          {
            id: 1,
            counter: materialTotalCount,
            description: countDescription,
          },
        ],
        children: isBarLoaded() && (
          <BarChart
            granularity={cardData.granularity}
            chartData={cardData.data}
            tipDescription={barChartTipDescription}
          />
        ),
      }}
      tableData={{
        isLoading: !isLoaded,
        rows: isLoaded && renderTableRows(),
        titles: [
          translate("entities.users.singular"),
          translate("common.email"),
          translate("common.accessIn"),
        ],
        empty: {
          variant: search && "search",
          message: emptyMessage,
          description: emptyDescription,
          icon: <Icon iconName="download" />,
        },
      }}
      tablePagination={{
        currentPage: pagination.page,
        lastPage: pagination.lastPage,
        handlePagination: page => fetchMaterialCountAccess(page),
      }}
    />
  );
}

const materialDetailsPropsTypes = {
  materialId: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  emptyMessage: PropTypes.string.isRequired,
  emptyDescription: PropTypes.string.isRequired,
  countDescription: PropTypes.string.isRequired,
  barChartTipDescription: PropTypes.string.isRequired,
};

MaterialDetailsFragment.propTypes = materialDetailsPropsTypes;

export default MaterialDetailsFragment;
