import React, { useState } from "react";
import PropTypes from "prop-types";
import { useSelector, useDispatch } from "react-redux";

// CUSTOM IMPORTS
import {
  sendRoomQuestion,
  clearRoomQuestion,
} from "~/modules/signed/modules/client/store/rooms/actions";
import { CustomModal, QuestionInput, SendQuestionButton } from "./styles";
import { isStringEmpty } from "~/shared/utils/strings";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import { Trans } from "~/shared/components/atoms";

function AskQuestionModal({ show, onClose, broadcastRoomId }) {
  // REDUCER
  const dispatch = useDispatch();
  const isQuestionSent = useSelector(
    state => state.rooms.transmissionRoom.isQuestionSent
  );

  // STATES
  const { translate } = useTranslation();
  const [question, setQuestion] = useState("");

  // FUNCTIONS
  const handleSendQuestion = () => {
    // Clear question input
    setQuestion("");

    if (isDataValid(broadcastRoomId))
      dispatch(sendRoomQuestion(broadcastRoomId, question));
  };

  const handleOnClose = () => {
    dispatch(clearRoomQuestion());

    onClose();
  };

  return (
    <CustomModal show={show} onClose={handleOnClose}>
      <h4 className="question-area-title">
        <Trans translationKey="components.askQuestionModal.title">
          Faça uma pergunta
        </Trans>
      </h4>

      <QuestionInput
        isQuestionSent={isQuestionSent}
        placeholder={
          isQuestionSent
            ? translate("components.askQuestionModal.questionSentMessage")
            : translate("components.askQuestionModal.textarea.placeholder")
        }
        value={question}
        onChange={e => setQuestion(e.target.value)}
        maxLength={230}
      />

      <SendQuestionButton
        active={!isStringEmpty(question) || isQuestionSent}
        type="button"
        onClick={isQuestionSent ? handleOnClose : handleSendQuestion}
      >
        {isQuestionSent
          ? translate("components.askQuestionModal.questionSentButton")
          : translate("components.askQuestionModal.questionButton")}
      </SendQuestionButton>
    </CustomModal>
  );
}

AskQuestionModal.propTypes = {
  onClose: PropTypes.func,
  show: PropTypes.any,
  broadcastRoomId: PropTypes.number.isRequired,
};

AskQuestionModal.defaultProps = {
  onClose: () => {},
  show: false,
};

export default AskQuestionModal;
