import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORT
import { Icon, Typography, Box } from "../../../atoms";
import { VideoPlayer, videoPlayerPropTypes } from "../../../molecules";
import { Container, CRUDOptionsMenu } from "./styles";

const extraPlayerCellPropTypes = {
  /** Render an title at top of the video */
  title: PropTypes.string.isRequired,

  /** Render an icon at rigth of title, you can see all <b>iconNames</b> into
   * <b>shared/utils/IconUtils</b>
   */
  iconName: PropTypes.string,

  /** Control all <b>VideoPlayer</b> props. See all props in <b>Molecules/Videoplayer</b> */
  videoPlayer: PropTypes.shape(videoPlayerPropTypes).isRequired,

  /** Callback that is triggered when user select <b>delete</b> option. */
  onDelete: PropTypes.func,

  /** Callback that is triggered when user select <b>edit</b> option. */
  onEdit: PropTypes.func,

  /** Name of the entity that this cell represent */
  entity: PropTypes.string,
};

const extraPlayerCellDefaultProps = {
  onDelete: () => {},
  onEdit: () => {},
  iconName: null,
  entity: null,
};

/** An Cell with an <b>VideoPlayer</b> and an title above the <b>VideoPlayer</b>, is commonly used
 * inside the <b>BroadcastRoomForm</b>
 */
function ExtraPlayerCell({
  title,
  iconName,
  entity,
  videoPlayer,
  onDelete,
  onEdit,
  ...rest
}) {
  return (
    <Container type="button" {...rest}>
      <Box
        display="flex"
        alignItens="center"
        marginBottom="10px"
        width="calc(100% - 30px)"
      >
        <Typography
          className="normal bold"
          maxLines={1}
          style={{ marginRight: 10 }}
        >
          {title}
        </Typography>

        {iconName && <Icon iconName={iconName} variant="accent" />}
      </Box>

      <VideoPlayer {...videoPlayer} />

      <CRUDOptionsMenu
        icon={<Icon iconName="menu" />}
        entity={entity}
        entityName={title}
        onDelete={onDelete}
        onEdit={onEdit}
        className="extra-player-cell-options"
      />
    </Container>
  );
}

ExtraPlayerCell.propTypes = extraPlayerCellPropTypes;

ExtraPlayerCell.defaultProps = extraPlayerCellDefaultProps;

export {
  ExtraPlayerCell,
  extraPlayerCellPropTypes,
  extraPlayerCellDefaultProps,
};
