import types from "./types";

// LIST
export function listRegistrationFields() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listRegistrationFieldsSuccessful({ registrationFields }) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { registrationFields },
  };
}
export function listRegistrationFieldsFailed() {
  return {
    type: types.LIST.FAILED,
  };
}
export function clearRegistrationFields() {
  return {
    type: types.LIST.CLEAR,
  };
}

// FORM
export function setState({ stateId }) {
  return {
    type: types.FORM.SET_STATE,
    payload: { stateId },
  };
}

export function setCity({ city }) {
  return {
    type: types.FORM.SET_CITY,
    payload: { city },
  };
}
