import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";

//* ACTIONS
import { clearCategories as clearBroadcastRooms } from "../../../store/broadcastRooms/actions";
import { clearMarketRooms } from "../../../store/marketRooms/actions";
import { clearNetworkRooms } from "../../../store/networkRooms/actions";
import { clearCertificates } from "../../../store/certificates/actions";

//* APIs
import { fetchBanners } from "~/modules/signed/modules/client/apis/banners";
import { useAsync, REQUEST_STATUS } from "~/shared/utils/hooks/useApi";
import { createActiveLog } from "~/modules/signed/modules/client/apis/activeLog";

//* PROVIDER
import { StickyNotesProvider } from "../pages/StickyNotes/hooks";

//* CUSTOM IMPORTS
import { Banners } from "~/shared/components/organisms";
import { HomeTemplate } from "~/shared/components/templates";
import { ACTIVE_LOG_TYPES } from "~/shared/utils/types";

const HomeContext = createContext(null);

const HomeProvider = ({ children, disableBanner }) => {
  const dispatch = useDispatch();

  //* STATES
  const { status, data: banners, run } = useAsync();
  const isBannerLoaded = status === REQUEST_STATUS.RESOLVED;

  //* FUNCTIONS
  React.useEffect(() => {
    return run(fetchBanners());
  }, [run]);

  /** Clear all reducers when this hooks unmounting */
  React.useEffect(() => {
    return () => {
      dispatch(clearBroadcastRooms());
      dispatch(clearMarketRooms());
      dispatch(clearNetworkRooms());
      dispatch(clearCertificates());
    };
  }, [dispatch]);

  const renderBanners = () => {
    if (!isBannerLoaded) return null;

    if (banners.length === 2) {
      return [...banners, ...banners];
    }

    return banners;
  };

  const handleBannerClick = slide => {
    createActiveLog({
      action: ACTIVE_LOG_TYPES.BANNER_CLICK,
      refId: slide.id,
    });
  };

  return (
    <HomeContext.Provider value={{ banners }}>
      <HomeTemplate
        bannerElement={
          !disableBanner && (
            <Banners
              isLoaded={isBannerLoaded}
              banners={renderBanners()}
              onClick={handleBannerClick}
            />
          )
        }
      >
        <StickyNotesProvider>{children}</StickyNotesProvider>
      </HomeTemplate>
    </HomeContext.Provider>
  );
};

HomeProvider.propTypes = {
  children: PropTypes.node.isRequired,
  disableBanner: PropTypes.bool,
};

HomeProvider.defaultProps = {
  disableBanner: false,
};

const useHome = () => {
  const context = useContext(HomeContext);

  if (!context) throw new Error("useHome must be used within HomeProvider");

  return context;
};

export { HomeProvider, useHome };
