import api from "~/shared/services/api";

export const listNotifications = ({ page }) =>
  Promise.resolve(api.get("/admin/notifications", { params: { page } }));

export const createNotification = ({ data }) =>
  Promise.resolve(api.post("/admin/notifications", data));

export const deleteNotification = ({ notificationId }) =>
  Promise.resolve(api.delete(`/admin/notifications/${notificationId}`));

export const updateNotification = ({ data, notificationId }) =>
  Promise.resolve(api.put(`/admin/notifications/${notificationId}`, data));

export const getNotificationTotalCount = ({ action, notificationId }) =>
  Promise.resolve(
    api.get("/admin/notifications/logs/count", {
      params: {
        action,
        notification_id: notificationId,
      },
    })
  );

export const sendNotification = notificationId =>
  Promise.resolve(api.patch(`/admin/notifications/send/${notificationId}`));

export const getNotificationChartCount = ({
  startDate,
  endDate,
  action,
  notificationId,
  granularity,
}) =>
  Promise.resolve(
    api.get("/admin/notifications/logs/chart", {
      params: {
        start_date: startDate,
        end_date: endDate,
        action,
        notification_id: notificationId,
        granularity,
      },
    })
  );

export const getNotificationsUsersCountAccess = ({
  startDate,
  endDate,
  page,
  notificationId,
  search,
  action,
}) =>
  Promise.resolve(
    api.get("/admin/notifications/logs/users", {
      params: {
        start_date: startDate,
        end_date: endDate,
        page,
        notification_id: notificationId,
        search,
        action,
      },
    })
  );
