import React from "react";
import { useLocation } from "react-router-dom";

// CUSTOM IMPORTS
import { hashNames } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { TagModal } from "~/modules/signed/modules/cms/components/modals/TagModal";
import SpeakerModal from "~/modules/signed/modules/cms/components/modals/SpeakerModal";
import { ExtraButtonModal } from "~/modules/signed/modules/cms/components/modals/ExtraButtonModal";
import ExtraPlayerModal from "~/modules/signed/modules/cms/components/modals/ExtraPlayerModal";
import GroupModal from "~/modules/signed/modules/cms/components/modals/GroupModal";
import RegistrationFieldModal from "~/modules/signed/modules/cms/components/modals/RegistrationFieldModal";
import ExtraEmbedModal from "~/modules/signed/modules/cms/components/modals/ExtraEmbedModal";

function HashListener() {
  const location = useLocation();

  // STATE
  const [currentHash, setCurrentHash] = React.useState(null);

  // FUNCTIONS
  React.useEffect(() => {
    if (isDataValid(location.hash)) {
      setCurrentHash(location.hash);
    }
  }, [location.hash]);

  function compareHash(hash) {
    if (isDataValid(currentHash)) if (currentHash === hash) return true;
    return false;
  }

  return (
    <>
      {compareHash(hashNames.TAG_MODAL) && (
        <TagModal
          tagType={isDataValid(location.state) && location.state.tagType}
          tag={isDataValid(location.state) && location.state.tag}
        />
      )}

      {compareHash(hashNames.CREATE_SPEAKER_MODAL) && <SpeakerModal />}
      {compareHash(hashNames.EDIT_SPEAKER_MODAL) && (
        <SpeakerModal speaker={location.state} />
      )}
      {compareHash(hashNames.EXTRA_BUTTON_MODAL) && (
        <ExtraButtonModal
          extraButton={
            isDataValid(location.state) && location.state.extraButton
          }
          refId={isDataValid(location.state) && location.state.refId}
          refType={isDataValid(location.state) && location.state.refType}
        />
      )}
      {compareHash(hashNames.EXTRA_PLAYER_MODAL) && (
        <ExtraPlayerModal
          extraPlayer={
            isDataValid(location.state) && location.state.extraPlayer
          }
          refId={isDataValid(location.state) && location.state.refId}
          refType={isDataValid(location.state) && location.state.refType}
        />
      )}
      {compareHash(hashNames.EXTRA_EMBED_MODAL) && (
        <ExtraEmbedModal
          extraEmbed={isDataValid(location.state) && location.state.extraEmbed}
        />
      )}
      {compareHash(hashNames.GROUP_MODAL) && (
        <GroupModal
          group={isDataValid(location.state) && location.state.group}
        />
      )}
      {compareHash(hashNames.REGISTRATION_FIELD_MODAL) && (
        <RegistrationFieldModal
          registrationField={
            isDataValid(location.state) && location.state.registrationField
          }
          editMode={isDataValid(location.state) && location.state.editMode}
        />
      )}
    </>
  );
}

export default HashListener;
