import React from "react";
import PropTypes from "prop-types";

//* ASSETS
import splash from "../../../assets/custom/splash.png";
import bgWide from "../../../assets/custom/wide_background.png";
import bgMobile from "../../../assets/custom/mobile_background.png";

//* CUSTOM IMPORTS
import { useWindow } from "../../../hooks/window";
import {
  Wrapper,
  Container,
  Background,
  Splash,
  Scroll,
  Children,
} from "./styles";

const authTemplatePropTypes = {
  children: PropTypes.element.isRequired,
};

/** The AuthTemplate is used inside the <b>auth</b> module, all <b>auth pages</b> is
 * inside this template.
 */
const AuthTemplate = ({ children }) => {
  const { isTablet, isMobile } = useWindow();

  return (
    <Wrapper id="auth-wrapper">
      <Container id="auth-container">
        <Scroll id="auth-scroll">
          <Children id="auth-children">{children}</Children>
        </Scroll>

        <Splash className="auth-container-splash" src={splash} />
      </Container>

      <Background
        className="auth-background"
        src={isTablet || isMobile ? bgMobile : bgWide}
      />
    </Wrapper>
  );
};

AuthTemplate.propTypes = authTemplatePropTypes;

export { AuthTemplate, authTemplatePropTypes };
