import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listNetworkSuccessful,
  showNetworkSuccessful,
  createNetworkRoomSuccessful,
  createNetworkRoomFailed,
  updateNetworkRoomFailed,
  updateNetworkRoomSuccessful,
  deleteNetworkRoomFailed,
  deleteNetworkRoomSuccessful,
  clearNetworkRoomForm,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/admin/networkroom");

    if (validResponseStatus(response.status)) {
      const networkRooms = response.data;

      yield put(listNetworkSuccessful(networkRooms));
    }
  } catch (err) {
    console.log(err);
  }
}

// SHOW
export function* show({ payload }) {
  try {
    const { networkRoomId } = payload;

    const response = yield call(api.get, `/admin/networkroom/${networkRoomId}`);

    if (validResponseStatus(response.status)) {
      const networkRoom = response.data;

      yield put(showNetworkSuccessful(networkRoom));
    }
  } catch (err) {
    console.log(err);
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { networkRoom } = payload;

    const response = yield call(api.post, "/admin/networkroom", networkRoom);

    if (validResponseStatus(response.status)) {
      const newNetworkRoom = response.data;

      yield put(createNetworkRoomSuccessful(newNetworkRoom));
    }

    yield put(clearNetworkRoomForm());
  } catch (err) {
    yield put(createNetworkRoomFailed());
    console.log(err);
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { networkRoom, networkRoomId } = payload;

    const response = yield call(
      api.put,
      `/admin/networkroom/${networkRoomId}`,
      networkRoom
    );
    if (validResponseStatus(response.status)) {
      const updatedNetworkRoom = response.data;

      yield put(updateNetworkRoomSuccessful(updatedNetworkRoom));
    }

    yield put(clearNetworkRoomForm());
  } catch (err) {
    yield put(updateNetworkRoomFailed());
    console.log(err);
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { networkRoomId } = payload;

    const response = yield call(
      api.delete,
      `/admin/networkroom/${networkRoomId}`
    );

    if (validResponseStatus(response.status)) {
      yield put(deleteNetworkRoomSuccessful(networkRoomId));
    }
  } catch (err) {
    yield put(deleteNetworkRoomFailed());
    console.log(err);
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.SHOW.REQUEST, show),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
]);
