import React from "react";
import PropTypes from "prop-types";

//* CUSTO M IMPORTS
import SingleBarLoader from "../../molecules/cellLoaders/SingleBarLoader";
import { useWindow } from "../../../hooks/window";
import { debounceEvent } from "../../../utils/forms";
import { Typography, Icon, Tooltip } from "../../atoms";
import {
  Modal,
  FooterButtons,
  VirtualizedTable,
  virtualizedTablePropTypes,
  footerButtonsPropTypes,
} from "../../molecules";
import { TableData, TableDataLoader } from "./styles";

const importErrorsModalPropTypes = {
  footerButtons: PropTypes.shape(footerButtonsPropTypes),

  /** The array to render the Table */
  data: virtualizedTablePropTypes.data,

  /** Render the titles of the list */
  titles: PropTypes.arrayOf(PropTypes.string).isRequired,

  /** Render an <b>Tooltip</b> in the end of each line with an error message */
  errorMessages: PropTypes.arrayOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.element])
  ),

  /** Callback that is triggered when user change the value of an input insde the
   * Table, (value, rowIndex, columnIndex) => {}
   */
  onChange: PropTypes.func,

  /** ID of the current rowData of the table that is loading, ID need to be like that
   * `${rowIndex}-${columnIndex}`
   */
  rowLoadingId: PropTypes.number,
};

const importErrorsDefaultProps = {
  footerButtons: null,
  errorMessages: [],
  data: [],
  onChange: () => {},
  rowLoadingId: null,
};

const ImportErrorsModal = ({
  footerButtons,
  data,
  titles,
  errorMessages,
  onChange,
  rowLoadingId,
  ...rest
}) => {
  const { windowWidth } = useWindow();

  //* FUNCTIONS
  const isTheLastColumn = columnIndex => {
    return columnIndex === data[0].length - 1;
  };

  const handleInputChange = (inputId, value, rowIndex, columnIndex) => {
    const input = document.getElementById(inputId);

    if (input) onChange(input.value, value, rowIndex, columnIndex);
  };

  const calculateDefaultWidht = () => {
    const modalWidth = windowWidth * 0.7 - 20;

    const lineWidth = modalWidth / data[0].length;

    return lineWidth >= 270 ? lineWidth - 20 : 250;
  };

  const renderTolltip = message =>
    message ? (
      <Tooltip
        message={message}
        variant="contained"
        icon={{ iconName: "alert", variant: "error" }}
        style={{ position: "absolute", right: 0 }}
      />
    ) : (
      <Icon
        iconName="correct"
        variant="correct"
        style={{ position: "absolute", right: 0 }}
      />
    );

  return (
    <Modal {...rest} style={{ width: "100%", maxWidth: "70vw", height: 360 }}>
      <div
        style={{
          display: "flex",
          width: "100%",
          flex: 1,
          flexDirection: "column",
        }}
      >
        <VirtualizedTable
          data={data}
          defaultWidth={calculateDefaultWidht()}
          renderCell={(value, rowIndex, columnIndex) => {
            const inputId = `INPUT-${rowIndex}-${columnIndex}`;
            const cellId = `${rowIndex}-${columnIndex}`;

            return (
              <div key={cellId}>
                {rowIndex === 0 && (
                  <Typography className="opaque small bold uppercase">
                    {titles[columnIndex]}
                  </Typography>
                )}

                {rowLoadingId === rowIndex ? (
                  <TableDataLoader>
                    <SingleBarLoader />
                  </TableDataLoader>
                ) : (
                  <>
                    <TableData
                      defaultWidth={calculateDefaultWidht()}
                      isErrored={value.error}
                      isEmpty={!value.value}
                      style={{
                        marginBottom: rowIndex === data.length - 1 ? 24 : 0,
                      }}
                    >
                      <input
                        id={inputId}
                        style={{ width: "100%" }}
                        type="text"
                        defaultValue={value.value || "Indisponível"}
                        onChange={debounceEvent(
                          () =>
                            handleInputChange(
                              inputId,
                              value,
                              rowIndex,
                              columnIndex
                            ),
                          1000
                        )}
                      />

                      {isTheLastColumn(columnIndex) &&
                        renderTolltip(errorMessages[rowIndex])}
                    </TableData>
                  </>
                )}
              </div>
            );
          }}
        />
      </div>

      {footerButtons && (
        <FooterButtons {...footerButtons} style={{ marginTop: 16 }} />
      )}
    </Modal>
  );
};

ImportErrorsModal.propTypes = importErrorsModalPropTypes;

ImportErrorsModal.defaultProps = importErrorsDefaultProps;

export {
  ImportErrorsModal,
  importErrorsModalPropTypes,
  importErrorsDefaultProps,
};
