import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  object: {
    data: {},
    isLoaded: false,
  },
};

export default function extraLinksReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { networkRooms } = action.payload;

        if (isDataValid(networkRooms)) draft.list.data = networkRooms;

        draft.list.isLoaded = true;

        break;
      }

      // SHOW
      case types.SHOW.SUCCESSFUL: {
        const { networkRoom } = action.payload;

        if (isDataValid(networkRoom)) draft.object.data = networkRoom;

        draft.object.isLoaded = true;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { networkRoom } = action.payload;

        if (isDataValid(networkRoom))
          draft.list.data = [networkRoom, ...draft.list.data];

        draft.list.isCreating = "FINISH";
        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { networkRoom } = action.payload;

        if (isDataValid(networkRoom)) {
          const networkRooms = draft.list.data;
          const networkRoomIndex = networkRooms.findIndex(
            s => s.id === networkRoom.id
          );

          networkRooms[networkRoomIndex] = networkRoom;

          draft.list.data = networkRooms;
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { networkRoomId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== networkRoomId
        );

        draft.list.data = filteredArray;

        draft.list.isDeleting = "FINISH";
        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // FORM
      case types.FORM.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;

        draft.object.data = {};
        draft.object.isLoaded = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isLoaded = false;

        draft.object.data = {};
        draft.object.isLoaded = false;

        break;
      }
      default:
    }
  });
}
