import { all, takeLatest, call, put } from "redux-saga/effects";
import { toast } from "react-toastify";

// CUSTOM IMPORTS
import api from "~/shared/services/api";
import history from "~/shared/services/history";
import { validResponseStatus } from "~/shared/utils/apis";
import {
  completeSignUpSuccessful,
  updateProfileSuccessful,
  updateAvatarSuccessful,
  clearUpdatingProfile,
  refreshProfileSuccessful,
  userAPIError,
} from "./actions";

// COMPLETE PROFILE
export function* completeSignUp({ payload }) {
  try {
    const { userData, userId } = payload;

    const response = yield call(api.put, `/users/${userId}`, userData);

    if (validResponseStatus(response.status)) {
      const user = response.data;

      yield put(completeSignUpSuccessful(user));

      toast.info("Cadastro completado!");

      // Redirect to home screen
      history.push("/home/broadcast-rooms");
    }
  } catch (err) {
    console.log(err);

    yield put(userAPIError());
  }
}

// UPDATE PROFILE
export function* updateProfile({ payload }) {
  try {
    const { userData, userId } = payload;

    const response = yield call(api.put, `/users/${userId}`, userData);

    if (validResponseStatus(response.status)) {
      const updatedUser = response.data;

      yield put(updateProfileSuccessful(updatedUser));

      toast.info("Perfil atualizado com sucesso!");
      history.goBack();
    }

    yield put(clearUpdatingProfile());
  } catch (err) {
    console.log(err);

    yield put(userAPIError());
  }
}

// UPDATE AVATAR
export function* updateAvatar({ payload }) {
  try {
    const { file, userId } = payload;

    const response = yield call(api.put, `/users/${userId}/avatar`, file, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });

    if (validResponseStatus(response.status)) {
      const updatedUser = response.data;

      yield put(updateAvatarSuccessful(updatedUser));
    }
  } catch (err) {
    console.log(err);

    yield put(userAPIError());
  }
}

export function* refreshProfile({ payload }) {
  try {
    const { userId } = payload;

    const response = yield call(api.get, `/users/${userId}`);

    if (validResponseStatus(response.status)) {
      const user = response.data;

      yield put(refreshProfileSuccessful(user));

      // const { registration_completed } = user;

      // if (!registration_completed) history.replace("register/complete");
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([
  takeLatest("@user/COMPLETE_SIGNUP", completeSignUp),
  takeLatest("@user/UPDATE_PROFILE", updateProfile),
  takeLatest("@user/UPDATE_AVATAR", updateAvatar),
  takeLatest("@user/REFRESH_PROFILE", refreshProfile),
]);
