import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { TAG_TYPES } from "~/shared/utils/types";
import { AdminFileInput } from "~/modules/signed/modules/cms/components/AdminFileInput";
import { Container, Input, TranslateInput, Tags } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";

function NetworkRoomInformation({
  selectedTags,
  setSelectedTags,
  networkRoom,
}) {
  const { translate } = useTranslation();

  return (
    <Container>
      <TranslateInput
        name="title"
        title={translate("entities.networkRooms.roomTitle.title")}
        placeholder={translate("entities.networkRooms.roomTitle.placeholder")}
      />

      <TranslateInput
        name="description"
        title={translate("entities.networkRooms.roomDescription.title")}
        placeholder={translate(
          "entities.networkRooms.roomDescription.placeholder"
        )}
        isTextArea
      />

      <Input
        name="video_call_url"
        title={translate("entities.networkRooms.roomLink.title")}
        placeholder={translate("entities.networkRooms.roomLink.placeholder")}
      />

      <AdminFileInput
        name="logo"
        heigth={128}
        width={279}
        initialValue={networkRoom && networkRoom.room.logo}
        style={{ marginBottom: 16 }}
      />

      <Tags
        type={TAG_TYPES.NETWORK_ROOM}
        selectedTags={selectedTags}
        setSelectedTags={setSelectedTags}
      />
    </Container>
  );
}

NetworkRoomInformation.propTypes = {
  selectedTags: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedTags: PropTypes.func.isRequired,
  networkRoom: PropTypes.object,
};

NetworkRoomInformation.defaultProps = {
  networkRoom: null,
};

export default NetworkRoomInformation;
