import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  getFullDateBR,
  getFullHours,
  isDateBefore,
} from "~/shared/utils/dates";
import { API_STATUS } from "~/shared/utils/apis";
import { Icon, Typography } from "~/shared/components/atoms";
import { CellList, Empty } from "~/shared/components/molecules";
import Loader from "~/shared/components/molecules/cellLoaders/CardLoader";
import { ScheduleCell } from "~/shared/components/organisms/cells";
import { useMySchedule } from "../../hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { MY_SCHEDULE_TYPES } from "~/shared/utils/types";

const meetingListPropTypes = {
  selectedMeetingId: PropTypes.object.isRequired,
  onItemClick: PropTypes.func.isRequired,
  search: PropTypes.string.isRequired,
  detailsComponent: PropTypes.oneOfType([undefined, null, PropTypes.element])
    .isRequired,
};

const MeetingList = ({
  search,
  selectedMeetingId,
  onItemClick,
  detailsComponent,
}) => {
  const { translate } = useTranslation();

  //* STATES
  const { meetings, paginateMyMeetings, listMyMeetings } = useMySchedule();
  const { itemId, status } = meetings;

  //* FUNCTIONS
  React.useEffect(() => {
    const fetch = async () => {
      await listMyMeetings(search);
    };

    fetch();
  }, [listMyMeetings, search]);

  const renderScheduledMeetings = day => (
    <CellList
      orientation="column"
      isLoaded
      gap={8}
      isDeleting={status === API_STATUS.DELETING}
      cellId={itemId}
      cellStyle={{ width: "100%" }}
      renderCellLoader={() => <Loader height={60} style={{ width: "100%" }} />}
      data={day.data}
      headerComponent={
        <Typography className="bold small opaque" style={{ marginBottom: 8 }}>
          {getFullDateBR(day.date)}
        </Typography>
      }
      renderCell={scheduledMeeting => {
        const { meeting, id } = scheduledMeeting;
        const { partner, start_date: startDate, end_date: endDate } = meeting;

        return (
          <>
            <ScheduleCell
              onClick={() =>
                onItemClick({
                  ...scheduledMeeting,
                  type: MY_SCHEDULE_TYPES.MEETING,
                })
              }
              isLive={
                !isDateBefore({
                  date: startDate,
                  dateToCompare: new Date(),
                }) && isDateBefore({ date: endDate, dateToCompare: new Date() })
              }
              variant={selectedMeetingId === id && "accent"}
              title={`${translate("common.meetingWith")} ${partner.title}`}
              disableMenu
              startTime={getFullHours(startDate)}
              endTime={getFullHours(endDate)}
              speakers={[{ id: partner.id, picture: partner.logo }]}
              liveTagContent={translate("common.happening now")}
            />

            {selectedMeetingId === id && detailsComponent}
          </>
        );
      }}
    />
  );

  const handleOnPaginate = () => {
    if (meetings.page < meetings.lastPage) {
      paginateMyMeetings(meetings.page + 1, search);
    }
  };

  return (
    <CellList
      gap={16}
      withScroll
      disableScrollListener={meetings.page >= meetings.lastPage}
      data={meetings.data}
      scrollStyle={{ padding: 8, maxHeight: "calc(100vh - 315px)" }}
      isLoaded={meetings.isLoaded}
      isPaging={meetings.status === API_STATUS.PAGING}
      listStyle={{ maxWidth: 450, margin: "0 auto" }}
      onBottomReached={handleOnPaginate}
      orientation="column"
      renderCell={renderScheduledMeetings}
      renderCellLoader={() => <Loader height={60} style={{ width: "100%" }} />}
      loaderListSize={4}
      emptyComponent={
        <Empty
          search={search}
          variant={search && "search"}
          icon={<Icon iconName="schedule" />}
          message={translate("entities.meetings.empty.message")}
          description={translate("entities.meetings.empty.description")}
        />
      }
    />
  );
};

MeetingList.propTypes = meetingListPropTypes;

export { MeetingList };
