import React from "react";
import {
  FaFacebook,
  FaLinkedin,
  FaInstagram,
  FaTwitter,
  FaGlobe,
} from "react-icons/all";

// CUSTOM IMPORTS
import { Container, SocialMidia } from "./styles";
import { isListValid, isDataValid } from "~/shared/utils/validations";

function SocialMidiasViewList({
  className,
  facebookUrl,
  instagramUrl,
  twitterUrl,
  linkedinUrl,
  websiteUrl,
  onClick,
}) {
  // SOCIAL MIDIA OBJECT
  const socialMidias = [
    { url: facebookUrl, name: "facebook" },
    { url: instagramUrl, name: "instagram" },
    { url: twitterUrl, name: "twitter" },
    { url: linkedinUrl, name: "linkedin" },
    { url: websiteUrl, name: "website" },
  ];

  // FUNCTIONS
  const renderCell = socialMidiaName => {
    // Return an svg based on social midia
    switch (socialMidiaName) {
      case "facebook":
        return <FaFacebook />;
      case "twitter":
        return <FaTwitter />;
      case "linkedin":
        return <FaLinkedin />;
      case "instagram":
        return <FaInstagram />;
      case "website": {
        return <FaGlobe />;
      }

      default:
        break;
    }
  };

  return (
    isListValid(socialMidias) && (
      <Container className={className} id="social-midias-view-list">
        {socialMidias.map(
          socialMidia =>
            isDataValid(socialMidia.url) && (
              <SocialMidia
                key={socialMidia.name}
                href={socialMidia.url}
                target="_blank"
                onClick={() => onClick(socialMidia.name)}
                className="social-midia-cell"
              >
                {renderCell(socialMidia.name)}
              </SocialMidia>
            )
        )}
      </Container>
    )
  );
}

export default SocialMidiasViewList;
