import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  sendMessage,
  sendPinnedMessage,
} from "~/modules/signed/modules/client/pages/BroadcastRoom/store/broadcastRoomChat/actions";

//* CUSTOM IMPORTS
import AskQuestionModal from "~/modules/signed/modules/client/components/modals/AskQuestionModal";
import { Typography, Trans } from "~/shared/components/atoms";
import { ChatInput, PinnedMessage } from "~/shared/components/molecules";
import { Container, Header, VirtualizedList } from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import { useSigned } from "~/modules/signed/hooks";
import { useSocket } from "~/modules/signed/hooks/webSocket";

function BroadcastRoomChat({ className, broadcastRoomId }) {
  //* REDUCERS
  const dispatch = useDispatch();
  const {
    data: chatMessages,
    isLoaded,
    blockedUsers,
    pinnedMessage,
  } = useSelector(state => state.wsBroadcastRoomChat.messages);
  const user = useSelector(state => state.user.profile.data);

  //* STATES
  const [isPinnedHide, setIsPinnedHide] = React.useState(false);
  const [isBottomReached, setIsBottomReached] = React.useState(true);
  const [replyMessage, setReplyMessage] = React.useState({
    id: null,
    message: null,
    messageTitle: null,
  });
  const [isQuestionModalVisible, setIsQuestionModalVisible] = React.useState(
    false
  );
  const { isMyUserAdmin } = useSigned();

  //* FUNCTIONS
  const { isConnected } = useSocket();
  const { translate } = useTranslation();

  React.useMemo(() => {
    //* *If receive an new pinnedMessage set hide pinnet to false */
    if (pinnedMessage) setIsPinnedHide(false);
  }, [pinnedMessage]);

  const handleMessageInputSubmit = message => {
    if (isConnected) {
      dispatch(
        sendMessage({
          message,
          user,
          broadcastRoomId,
          messageRepliedId: replyMessage.id,
        })
      );

      setReplyMessage({
        id: null,
        message: null,
        messageTitle: null,
      });

      setTimeout(() => setIsBottomReached(true), 1);
    }
  };

  const handleUnPinMessage = () =>
    dispatch(
      sendPinnedMessage({
        broadcastRoomId,
        messageId: pinnedMessage.id,
      })
    );

  //* If user id is inside blocke users return TRUE
  const isUserBlocked = () => {
    if (!isConnected) return true;

    return blockedUsers.find(blockedUser => blockedUser === user.id);
  };

  return (
    <Container className={className}>
      <Header>
        <Typography className="bold tinyTitle">
          <Trans translationKey="entities.broadcastRooms.chat.title">
            Chat da transmissão
          </Trans>
        </Typography>
      </Header>

      <VirtualizedList
        isBottomReached={isBottomReached}
        setIsBottomReached={setIsBottomReached}
        onQuestionButtonClick={() => setIsQuestionModalVisible(true)}
        userId={user.id}
        broadcastRoomId={broadcastRoomId}
        className="chat-message-window"
        chatMessages={chatMessages}
        isLoaded={isLoaded}
        onReplyClick={chatMessage => {
          setReplyMessage({
            id: chatMessage.id,
            message: chatMessage.message,
            messageTitle: chatMessage.user.name,
          });

          const chatInput = document.getElementById("chat-text-area");

          if (chatInput) chatInput.focus();
        }}
      />

      {pinnedMessage && (
        <PinnedMessage
          isHide={isPinnedHide}
          onHide={() => setIsPinnedHide(true)}
          deleteButton={
            isMyUserAdmin && {
              onClick: handleUnPinMessage,
              title: translate("common.unPinMessage"),
            }
          }
          onUnHide={() => setIsPinnedHide(false)}
          hidePinnedMessage={translate("components.pinnedMessage.hideMessage")}
          message={pinnedMessage.message}
          isPinned
          style={{ marginBottom: 12 }}
        />
      )}

      {isLoaded && (
        <ChatInput
          limit={200}
          spamValue={5}
          isBlocked={isUserBlocked()}
          onSubmit={handleMessageInputSubmit}
          replyMessageCard={{
            message: replyMessage.message,
            messageTitle: replyMessage.messageTitle,
            onClose: () =>
              setReplyMessage({ message: null, messageTitle: null }),
          }}
          placeholder={
            isUserBlocked()
              ? translate(
                  !isConnected
                    ? "common.youAreDisconnected"
                    : "common.youAreBlocked"
                )
              : translate("common.writeMessage")
          }
        />
      )}

      <AskQuestionModal
        show={isQuestionModalVisible}
        onClose={() => setIsQuestionModalVisible(false)}
        broadcastRoomId={broadcastRoomId}
      />
    </Container>
  );
}

BroadcastRoomChat.propTypes = {
  className: PropTypes.string.isRequired,
  broadcastRoomId: PropTypes.number.isRequired,
};

export default BroadcastRoomChat;
