import React from "react";
import PropTypes from "prop-types";
import { ThemeContext } from "styled-components";

//* CUISTOM IMPORTS
import { stopPropagation } from "../../../utils/events";
import {
  Avatar,
  Icon,
  badgePropTypes,
  avatarPropTypes,
  avatarDefaultProps,
  Typography,
} from "../../atoms";
import { Card, Badge, CloseButton } from "./styles";

const avatarFramePropTypes = {
  /** The URL for the image  */
  avatar: PropTypes.string,

  variant: PropTypes.oneOf(["accent", "default"]),

  counter: badgePropTypes.counter,

  badgeColor: badgePropTypes.color,

  /** Callback that is triggered when user clicks into <b>CloseButton</b>.
   * If this props has no implementation, remove the <b>CloseButton</b>.
   */
  onClose: PropTypes.func,

  avatarSize: avatarPropTypes.size,

  label: PropTypes.string,

  subTitle: PropTypes.string,

  frame: avatarPropTypes.frame,
};

const avatarFrameDefaultProps = {
  avatar: null,
  variant: "default",
  counter: null,
  badgeColor: null,
  onClose: null,
  avatarSize: avatarDefaultProps.size,
  label: null,
  frame: null,
  subTitle: null,
};

/** This is component is an <b>Avatar</b> component, sinde an Frame. */
const AvatarFrame = ({
  avatar,
  variant,
  counter,
  badgeColor,
  onClose,
  avatarSize,
  label,
  subTitle,
  frame,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);

  return (
    <Card variant={variant} {...rest}>
      <Avatar
        src={avatar}
        size={avatarSize}
        frame={frame}
        badgeOutlineColor={variant === "accent" && colors.accent}
        style={{ margin: "0 auto" }}
      />

      {label && (
        <Typography
          maxLines={2}
          className="bold"
          style={{ textAlign: "center", marginTop: 8 }}
        >
          {label}
        </Typography>
      )}

      {subTitle && (
        <Typography
          maxLines={1}
          className="opaque"
          style={{ textAlign: "center" }}
        >
          {subTitle}
        </Typography>
      )}

      {counter > 0 && (
        <Badge
          counter={counter}
          size={24}
          color={badgeColor || colors.accent}
          withBoxShadow
          disableOutline
        />
      )}

      {onClose && (
        <CloseButton
          className="avatar-frame-close-button"
          variant="surface"
          size={16}
          onClick={e => stopPropagation(e, onClose)}
        >
          <Icon iconName="close" size={16} />
        </CloseButton>
      )}
    </Card>
  );
};

AvatarFrame.propTypes = avatarFramePropTypes;

AvatarFrame.defaultProps = avatarFrameDefaultProps;

export { AvatarFrame, avatarFramePropTypes };
