import styled, { css, keyframes } from "styled-components";

//* CUSTOM IMPORTS
import { layers, medias } from "../../../styles";
import { Box, Typography } from "../../atoms";
import { fadeIn } from "../../../styles/animations";

const logoAnimationIn = keyframes`
    from{
      left: 140px;
    }

    to{
      left: 50%;
    }
`;

const logoAnimationOut = keyframes`
    from{
      position: absolute;
      left: 50%;
    }

    to{
      left: 78px;
    }
`;

export const Container = styled.div`
  width: 100%;
  height: 80px;
  position: fixed;
  z-index: ${layers.header};
  display: flex;
  align-items: center;
  background: ${props => props.theme.header.background};
  box-shadow: 0 6px 6px ${props => props.theme.colors.shadow};
  transition: height 0.5s ease-in-out;
  padding: 8px 24px 8px 8px;

  .header-left-button {
    .button-tip {
      transform: translate(-8%, 100%);
    }
  }

  @media (max-width: ${medias.wide}) {
    .header-user-infos {
      display: none;
    }
  }

  @media (max-width: ${medias.largeTablet}) {
    height: 60px;
    padding: 8px;

    .MuiButtonBase-root {
      padding: 12px;
    }

    .header-left-buttons,
    .header-username,
    .header-title {
      display: none;
    }

    .header-logo {
      left: 48px;
    }
  }

  ${props => {
    switch (props.variant) {
      case "return":
        return css`
          .header-title {
            animation: ${fadeIn} 1.5s ease-in-out;
          }

          .header-left-buttons {
            display: none;
          }

          .header-logo {
            left: 50%;
            animation: ${logoAnimationIn} 0.3s ease-in-out;
            position: absolute;
            transform: translateX(-50%);
          }
        `;

      default:
        return css`
          .header-title {
            display: none;
          }

          .header-left-buttons {
            position: relative;
            left: 160px;
            animation: ${fadeIn} 1s ease-in-out;
          }

          .header-logo {
            animation: ${logoAnimationOut} 0.3s ease-in-out;
          }
        `;
    }
  }};
`;

export const Title = styled(Typography)``;

export const RightBox = styled(Box)`
  display: flex;
  align-items: center;
`;

export const LeftBox = styled(Box)`
  display: flex;
  align-items: center;
  height: 100%;
  flex: 1;
`;

export const Logo = styled.img`
  cursor: pointer;
  height: 70%;
  position: absolute;
  left: 78px;
`;

export const SlapHole = styled.div`
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;

  @media (max-width: ${medias.largeTablet}) {
    height: 60px;
  }
`;
