import styled, { css } from "styled-components";

export const Container = styled.div`
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  position: relative;
  background-color: ${props => props.color};
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0px 5px 6px ${props => props.theme.colors.shadow};
  padding: 16px;

  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 13%;
    height: 18%;
    background: ${props => props.color};
    box-shadow: 3px -2px 10px ${props => props.theme.colors.shadow},
      inset 15px -15px 15px ${props => props.theme.colors.shadow};
    z-index: 2;
    transform: skewX(25deg);
    left: 0;
    bottom: -7%;
  }

  ${props => {
    switch (props.foldPosition) {
      case "topRight":
        return css`
          border-radius: 0 22.5%/15%;
          border-bottom-left-radius: 0;

          &:before {
            left: unset;
            bottom: unset;
            top: -6%;
            right: -1%;
            transform: rotate(160deg);
          }
        `;

      case "topLeft":
        return css`
          border-radius: 22.5%/15% 0 0 0;

          &:before {
            left: unset;
            bottom: unset;
            top: -6%;
            left: -1%;
            transform: rotate(90deg);
          }
        `;

      case "bottomRight":
        return css`
          border-radius: 0 0 15%/22.5% 0;

          &:before {
            left: unset;
            top: unset;
            bottom: -4.5%;
            right: -3%;
            transform: rotate(-111deg);
          }
        `;

      default:
        return css`
          border-radius: 0 0 0 15%/22.5%;
        `;
    }
  }}
`;
