import types from "./types";

// CREATE
export function createRate(rateData) {
  return {
    type: types.CREATE.REQUEST,
    payload: { rateData },
  };
}
export function createRateSuccessful() {
  return {
    type: types.CREATE.SUCCESSFUL,
  };
}
export function createRateFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// SHOW
export function showRate(roomId, refId, type) {
  return {
    type: types.SHOW.REQUEST,
    payload: { roomId, refId, type },
  };
}
export function showRateSuccessful(rate) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { rate },
  };
}

// CLEAR
export function clearRate() {
  return {
    type: types.CLEAR,
  };
}
