import React from "react";
import PropTypes from "prop-types";
import { MdMoreVert, MdBlock, MdDelete, AiFillPushpin } from "react-icons/all";
import { useDispatch } from "react-redux";
import { ThemeContext } from "styled-components";

//* ACTIONS
import {
  sendBroadcastRoomChatReaction,
  removeBroadcastRoomChatReaction,
} from "~/modules/signed/modules/client/pages/BroadcastRoom/store/reactions/actions";
import {
  openChatMessageOptions,
  closeChatMessageOptions,
  blockUserChat,
  deleteMessage,
  sendPinnedMessage,
} from "~/modules/signed/modules/client/pages/BroadcastRoom/store/broadcastRoomChat/actions";

//* CUSTOM IMPORTS
import { ChatMessageCell } from "~/shared/components/organisms/cells/ChatMessageCell";
import { isDataValid } from "~/shared/utils/validations";
import { Trans } from "~/shared/components/atoms";

function ChatMessage({
  chatMessage,
  isAdmin,
  broadcastRoomId,
  userId,
  ...rest
}) {
  const {
    id: messageId,
    message,
    user,
    created_at: createdAt,
    __meta__: reaction,
    repliedMessage,
  } = chatMessage;
  const { colors } = React.useContext(ThemeContext);

  //* REDUCERS
  const dispatch = useDispatch();

  const isReactionValid = isDataValid(reaction);

  //* STATES
  const [isLiked, setIsLiked] = React.useState(() => {
    if (!isReactionValid) return false;

    //* *Check if userLogged has already liked this message  */
    return reaction.users_already_liked.includes(userId);
  });
  const [likeCount, setLikeCount] = React.useState(
    isReactionValid ? Number(reaction.reactions_count) : 0
  );

  React.useMemo(() => {
    if (isDataValid(reaction)) {
      const { reactions_count } = reaction;

      setLikeCount(Number(reactions_count));
    }
  }, [reaction]);

  //* FUNCTIONS
  const fillItems = () => {
    // This action is needed to receive new chat messages even with options opened
    dispatch(openChatMessageOptions());

    const items = [
      {
        id: 2,
        name: (
          <Trans translationKey="entities.messages.delete">
            Excluir mensagem
          </Trans>
        ),
        icon: <MdDelete />,
        function: () => dispatch(deleteMessage({ messageId })),
        color: colors.redWrong,
      },
      {
        id: 3,
        name: (
          <Trans translationKey="entities.messages.pin">Fixar Mensagem</Trans>
        ),
        icon: <AiFillPushpin />,
        function: () =>
          dispatch(sendPinnedMessage({ messageId, broadcastRoomId })),
      },
    ];

    //* * If is not the same user message display block user option into admin menu */
    if (userId !== user.id)
      items.unshift({
        id: 1,
        name: (
          <Trans translationKey="entities.users.block">Bloquear usuário</Trans>
        ),
        icon: <MdBlock />,
        color: colors.redWrong,
        function: () =>
          dispatch(blockUserChat({ userId: user.id, broadcastRoomId })),
      });

    return items;
  };

  const sendUnlike = () =>
    dispatch(
      removeBroadcastRoomChatReaction({
        broadcastRoomId,
        messageId,
      })
    );

  const sendLike = () =>
    dispatch(
      sendBroadcastRoomChatReaction({
        broadcastRoomId,
        messageId,
        emojiType: ":thumbsup:",
      })
    );

  const handleLikeButtonClick = () => {
    setIsLiked(!isLiked);
    setLikeCount(isLiked ? likeCount - 1 : likeCount + 1);

    if (isLiked) {
      sendUnlike();
    } else {
      sendLike();
    }
  };

  return (
    <ChatMessageCell
      {...rest}
      avatar={user.avatar}
      messageTitle={user.name}
      createdAt={createdAt}
      message={message}
      hasHighlight={user.has_highlight}
      isLiked={isLiked}
      likes={likeCount}
      onLikeButtonClick={handleLikeButtonClick}
      simpleMenu={
        isAdmin && {
          fillItems,
          onClose: () => dispatch(closeChatMessageOptions()),
          buttonIcon: <MdMoreVert />,
        }
      }
      replyMessageCard={
        repliedMessage
          ? {
              message: repliedMessage.message,
              messageTitle: repliedMessage.user.name,
              hasHighlight: repliedMessage.user.has_highlight,
            }
          : null
      }
    />
  );
}

ChatMessage.propTypes = {
  broadcastRoomId: PropTypes.number.isRequired,
  chatMessage: PropTypes.object.isRequired,
  onMeasure: PropTypes.func,
  userId: PropTypes.number.isRequired,
  isAdmin: PropTypes.bool.isRequired,
};

ChatMessage.defaultProps = {
  onMeasure: () => {},
};

export default ChatMessage;
