import React from "react";
import PropTypes from "prop-types";
import { Trans as I18NTrans } from "react-i18next";

const transPropTypes = {
  /** Translates according to the standard browser language */
  translationKey: PropTypes.string.isRequired,
  /** Text to facilitate understanding of the code  */
  children: PropTypes.string.isRequired,
};
/** */
function Trans({ translationKey, children, ...rest }) {
  return (
    <I18NTrans i18nKey={translationKey} {...rest}>
      {children}
    </I18NTrans>
  );
}

Trans.propTypes = transPropTypes;

export { Trans };
