import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

// APIS
import {
  getDownloadableMaterial,
  showDownloadableMaterial,
} from "~/modules/signed/modules/client/apis/downloadMaterials";

//* CUSTOM IMPORTS
import {
  openDownloadMaterials,
  openFolderDetailsMaterials,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";

const UserDownloadMaterialsContext = createContext(null);

const UserDownloadMaterialsProvider = ({ children }) => {
  const { translate } = useTranslation();

  // STATES
  const [materials, setMaterials] = React.useState(null);
  const [materialLink, setMaterialLink] = React.useState(null);
  const [folders, setFolders] = React.useState([]);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);
  const [folderTitle, setFolderTitle] = React.useState(null);
  const [materialFolder, setMaterialFolder] = React.useState(null);
  const [folderPaths, setFolderPaths] = React.useState([
    {
      id: 1,
      name: translate("entities.downloadMaterials.downloadableMaterials"),
      function: openDownloadMaterials,
    },
  ]);
  const [selectedFolderPath, setSelectedFolderPath] = React.useState(0);
  const [copyList, setCopyList] = React.useState([
    {
      id: 1,
      name: translate("entities.downloadMaterials.downloadableMaterials"),
      function: openDownloadMaterials,
    },
  ]);

  const listMaterial = React.useCallback(id => {
    const list = async () => {
      await getDownloadableMaterial({
        type: "file",
        folderId: id,
      }).then(response => {
        setMaterials(response.data);
        setIsLoaded(true);
      });
    };

    list();
  }, []);

  const listFolders = React.useCallback(id => {
    const list = async () => {
      await getDownloadableMaterial({
        type: "folder",
        folderId: id,
      }).then(response => {
        setFolders(response.data);
        setIsLoaded(true);
      });
    };

    list();
  }, []);

  const showMaterial = React.useCallback(id => {
    const show = async () => {
      await showDownloadableMaterial(id).then(response => {
        // const url = window.URL.createObjectURL(new Blob([response.data.file]));
        const link = document.createElement("a");
        link.href = response.data.file;
        setMaterialLink(response.data.file);
        link.setAttribute(
          "download",
          `${response.data.title}.${response.data.file_type}`
        );
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      });
      setIsLoading(false);
    };

    show();
  }, []);

  const showFolder = React.useCallback(id => {
    const show = async () => {
      setIsLoading(true);
      await showDownloadableMaterial(id).then(response => {
        setMaterialFolder(response.data);
        setFolderTitle(response.data.title);
      });
      setIsLoading(false);
    };

    show();
  }, []);

  const renderFolderPaths = React.useCallback(
    (folderId, title) => {
      const folderIndex = copyList.findIndex(obj => obj.id === folderId);
      let newFolderList = [...copyList];

      if (folderIndex !== -1) {
        newFolderList.splice(
          folderIndex + 1,
          newFolderList.length - (folderIndex - 1)
        );
      } else {
        newFolderList = [
          ...copyList,
          {
            id: folderId,
            name: title,
            function: () => openFolderDetailsMaterials(folderId),
          },
        ];
      }

      setCopyList(newFolderList);

      setFolderPaths(() => {
        if (newFolderList.length > 4) {
          const list = newFolderList.slice(-4);
          setSelectedFolderPath(list.length - 1);

          return list;
        }
        setSelectedFolderPath(newFolderList.length - 1);

        return newFolderList;
      });
    },
    [copyList]
  );

  return (
    <UserDownloadMaterialsContext.Provider
      value={{
        // STATES
        materials,
        materialLink,
        folders,
        isLoaded,
        isLoading,
        folderPaths,
        selectedFolderPath,
        folderTitle,
        materialFolder,

        // FUNCTIONS
        showFolder,
        setFolderTitle,
        setMaterialFolder,
        renderFolderPaths,
        setFolderPaths,
        showMaterial,
        listMaterial,
        listFolders,
      }}
    >
      {children}
    </UserDownloadMaterialsContext.Provider>
  );
};

UserDownloadMaterialsProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

function useDownloadMaterial() {
  const context = useContext(UserDownloadMaterialsContext);

  if (!context)
    throw new Error(
      "useDownloadMaterial must be used within DownloadMaterialsContext"
    );

  return context;
}

export { UserDownloadMaterialsProvider, useDownloadMaterial };
