import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* APIs
import { createBroadcastRoomScheduleRate } from "~/modules/signed/modules/client/apis/broadcastRooms/schedule/rate";

//* ACTIONS
import {
  fetchRoomSchedule,
  clearRoomSchedule,
} from "~/modules/signed/modules/client/store/schedules/actions";

//* CUSTOM IMPORTS
import { isDateAfter, isDateBefore } from "~/shared/utils/dates";
import ScheduleViewList from "../ScheduleViewList";
import { useTranslation } from "~/shared/hooks/translation";

function BroadcastRoomScheduleList({ className, broadcastRoomId }) {
  //* REDUCER
  const dispatch = useDispatch();
  const roomScheduleReducer = useSelector(
    state => state.schedules.roomSchedule
  );
  const { data: roomSchedule, isLoaded } = roomScheduleReducer;

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (
      isMounted === true &&
      broadcastRoomId !== null &&
      broadcastRoomId !== undefined
    )
      dispatch(fetchRoomSchedule(broadcastRoomId));

    return () => {
      dispatch(clearRoomSchedule());
      isMounted = false;
    };
  }, [dispatch, broadcastRoomId]);

  const handleOnEvaluate = async (rate, scheduleId) => {
    await createBroadcastRoomScheduleRate({ rate, scheduleId });
  };

  return (
    isLoaded &&
    roomSchedule.length > 0 && (
      <ScheduleViewList
        roomId={broadcastRoomId}
        title={translate("entities.broadcastRooms.broadcastRoomSchedule")}
        className={className}
        liveSchedule={roomSchedule.filter(
          s =>
            isDateAfter({ date: s.start_time, dateToCompare: new Date() }) &&
            isDateBefore({ date: s.end_time, dateToCompare: new Date() })
        )}
        schedule={roomSchedule.filter(
          s =>
            isDateAfter({ date: s.end_time, dateToCompare: new Date() }) ||
            isDateBefore({ date: s.start_time, dateToCompare: new Date() })
        )}
        onEvaluate={(rate, scheduleId) => handleOnEvaluate(rate, scheduleId)}
      />
    )
  );
}

BroadcastRoomScheduleList.propTypes = {
  broadcastRoomId: PropTypes.number.isRequired,
  className: PropTypes.string,
};

BroadcastRoomScheduleList.defaultProps = {
  className: "room-schedule-list",
};

export default BroadcastRoomScheduleList;
