import React from "react";
import PropTypes from "prop-types";
import { MdMoreHoriz } from "react-icons/md";
import { useSelector } from "react-redux";

// CUSTOM IMPORTS
import { getDistanceToNow } from "~/shared/utils/dates";
import {
  Container,
  Message,
  UserName,
  UserAvatar,
  CreatedAt,
  Options,
} from "./styles";

function CommentCell({ comment, onDelete }) {
  const userLogged = useSelector(state => state.user.profile.data);
  const { id, message, user, created_at: createdAt } = comment;

  const isMyComment = userLogged.id === user.id;

  return (
    <Container>
      <UserAvatar src={user.avatar} />

      <div>
        <UserName maxLines={1}>{user.name}</UserName>
        <Message>{String(message).trim()}</Message>
        <CreatedAt>{getDistanceToNow(createdAt)}</CreatedAt>
      </div>

      {isMyComment && (
        <Options
          icon={<MdMoreHoriz />}
          entity="Comentário"
          entityName="seu comentário"
          onlyDelete
          onDelete={() => onDelete(id)}
        />
      )}
    </Container>
  );
}

CommentCell.propTypes = {
  comment: PropTypes.object.isRequired,
  onDelete: PropTypes.func,
};

CommentCell.defaultProps = {
  onDelete: () => {},
};

export default CommentCell;
