import React from "react";
import { useParams } from "react-router-dom";

//* CUSTOM IMPORTS
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { NotificationDetailsFragment } from "../../components/NotificationDetailsFragment";
import { useTranslation } from "~/shared/hooks/translation";

const NotificationOpened = () => {
  const { translate } = useTranslation();

  const { notificationId } = useParams();

  return (
    <NotificationDetailsFragment
      notificationId={notificationId}
      action={NOTIFICATION_TYPES.OPENED}
      title={translate("entities.notifications.opened.plural")}
      emptyMessage={translate("entities.notifications.opened.empty.message")}
      emptyDescription={translate(
        "entities.notifications.opened.empty.description"
      )}
      countDescription={translate("common.peopleWhoOpened")}
      barChartTipDescription={translate("common.openings")}
    />
  );
};

export default NotificationOpened;
