import React from "react";
import { MdEdit } from "react-icons/md";
import { Form } from "@unform/web";

// APIs
import {
  enableRegistration,
  getIsRegistrationEnabled,
} from "~/modules/signed/modules/cms/modules/admin/apis/settings";

import {
  createVariable,
  getVariableUser,
} from "~/modules/signed/modules/cms/modules/admin/apis/variables";

// CUSTOM IMPORTS
import { PAGE_VIEW } from "~/shared/utils/types";
import { openRegistrationFields } from "~/shared/routes/navigation";
import { Container, Fieldset, EditFieldsButton, Dropdown } from "./styles";
import { Checkbox, Trans } from "~/shared/components/atoms";
import { useTranslation } from "~/shared/hooks/translation";

const options = [
  {
    id: 1,
    name: PAGE_VIEW.LOGIN.name,
    value: PAGE_VIEW.LOGIN.value,
  },
  {
    id: 2,
    name: PAGE_VIEW.SIGN_UP.name,
    value: PAGE_VIEW.SIGN_UP.value,
  },
];

function GeneralTab() {
  const { translate } = useTranslation();

  // STATES
  const [isChecked, setIsChecked] = React.useState(false);
  const [option, setOption] = React.useState(null);
  const [isLoaded, setIsLoaded] = React.useState(false);

  // FUNCTIONS
  React.useState(() => {
    const fetch = async () => {
      await getIsRegistrationEnabled().then(response => {
        setIsChecked(response.data.register);
      });
    };

    fetch();
  }, []);

  async function handleCheckboxClick() {
    await enableRegistration({ registerStatus: !isChecked });

    setIsChecked(!isChecked);
  }

  React.useEffect(() => {
    const fetch = async () => {
      await getVariableUser({ key: "optionPage" }).then(response => {
        setTimeout(() => {
          setOption(response.data.value);
          setIsLoaded(true);
        }, 30);
      });
    };
    fetch();
  }, []);

  async function handleDropDownSelect(value) {
    await createVariable({
      key: "optionPage",
      value,
    });

    setOption(value);
  }

  return (
    <Container>
      <Fieldset>
        <h5>
          <Trans translationKey="common.accessSettings">
            Configurações de acesso
          </Trans>
        </h5>

        <div id="checkbox-area">
          <Checkbox checked={isChecked} onClick={handleCheckboxClick} />

          <p>
            <Trans translationKey="entities.users.canRegister">
              Usuários podem se cadastrar
            </Trans>
          </p>
        </div>
      </Fieldset>
      {isChecked && isLoaded && (
        <Fieldset>
          <Form>
            <h5>
              <Trans translationKey="common.selectAccessAcreen">
                Selecione a tela de acesso principal
              </Trans>
            </h5>
            <Dropdown
              noRegistration
              name="option_type"
              options={options}
              placeholder="Selecione uma opção"
              initialValue={
                option === "login"
                  ? translate("common.loginScreen")
                  : translate("common.signUpScreen")
              }
              onChange={handleDropDownSelect}
            />
          </Form>
        </Fieldset>
      )}

      <Fieldset>
        <h5>
          <Trans translationKey="entities.registrationFields.plural">
            Campos de cadastro
          </Trans>
        </h5>

        <EditFieldsButton onClick={openRegistrationFields} icon={<MdEdit />}>
          <Trans translationKey="entities.registrationFields.edit">
            Editar campos de cadastro
          </Trans>
        </EditFieldsButton>
      </Fieldset>
    </Container>
  );
}

export { GeneralTab };
