import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography } from "../../../atoms";
import { SimpleCell } from "../../SimpleCell";
import { CustomButtonContent, Touchable } from "./styles";

const posterCellPropTypes = {
  title: PropTypes.string.isRequired,

  description: PropTypes.string,

  footerDescription: PropTypes.string,

  /** Callback that is triggered when user clicks into <b>EditButton</b>.
  if this props has some data render an <b>CRUDOptions</b> inside the card
  */
  onEdit: PropTypes.func,

  /** Callback that is triggered when user clicks into <b>DeleteButton</b>.
  if this props has some data render an <b>CRUDOptions</b> inside the card
  */
  onDelete: PropTypes.func,

  /** Render a element, default is null */
  customButton: PropTypes.element,
};

const posterCellDefaultProps = {
  description: null,
  footerDescription: null,
  onEdit: null,
  onDelete: null,
  customButton: null,
};

/** PosterCell use <b>SimpleCell</b> props, to see all props see <b>Organisms/SimpleCell</b> */
const PosterCell = ({
  title,
  description,
  footerDescription,
  onEdit,
  onDelete,
  customButton,
  ...rest
}) => (
  <Touchable disableHoverEffect>
    <SimpleCell
      {...rest}
      cardStyle={{ height: 160, width: 300 }}
      style={{ height: 160, width: 300 }}
      onDelete={onDelete}
      onEdit={onEdit}
      disableMenu={!onDelete && !onEdit}
    >
      <Typography maxLines={3} className="subTitle bold">
        {title}
      </Typography>

      {description && (
        <Typography
          maxLines={3}
          style={{ marginTop: 8 }}
          className="opaque normal"
        >
          {description}
        </Typography>
      )}

      {footerDescription && (
        <Typography
          maxLines={2}
          style={{ marginTop: 16 }}
          className="opaque normal bold"
        >
          {footerDescription}
        </Typography>
      )}

      {customButton && (
        <CustomButtonContent className="poster-list-custom-button">
          {customButton}
        </CustomButtonContent>
      )}
    </SimpleCell>
  </Touchable>
);

PosterCell.propTypes = posterCellPropTypes;

PosterCell.defaultProps = posterCellDefaultProps;

export { PosterCell, posterCellPropTypes, posterCellDefaultProps };
