import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import {
  SimpleMenu as SimpleMenuC,
  ReplyMessageCard as ReplyMessageCardC,
} from "../../../molecules";
import { Card as CardC, Typography } from "../../../atoms";

export const Card = styled(CardC)`
  padding: 8px;
  width: fit-content;

  ${props => {
    switch (props.variant) {
      case "accent":
        return css`
          background-color: ${props.theme.colors.accent};
          color: ${props.theme.colors.surface};

          svg {
            color: ${props.theme.colors.surface};
          }

          .talk-message-time {
            color: ${props.theme.colors.surface};
          }

          .talk-message-menu {
            background-color: ${props.theme.colors.accent};
          }
        `;

      default:
        return css`
          .talk-message-menu {
            background-color: ${props.theme.colors.surface};
          }
        `;
    }
  }};

  .talk-message-menu {
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &:hover {
    .talk-message-menu {
      opacity: 1;
    }
  }
`;

export const Message = styled(Typography)`
  margin-right: 16px;
  word-break: break-word;
  white-space: pre-line;
  display: flex;
  color: ${props => props.variant === "accent" && props.theme.colors.surface};
`;

export const ReplyMessageCard = styled(ReplyMessageCardC)`
  margin-bottom: 8px;
`;

export const SimpleMenu = styled(SimpleMenuC)`
  position: absolute;
  top: -8px;
  right: 0;
  border-radius: 50%;
`;
