import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIs
import {
  listBroadcastCategories,
  deleteBroadcastRoom,
  updateCategoryBroadcastroom,
  reorderBroadcastRooms,
} from "~/modules/signed/modules/cms/modules/admin/apis/broadcastRooms";

import {
  openCreateBroadcastRoom,
  openEditBroadcastRoom,
} from "~/shared/routes/navigation";
import { reorderCategory } from "../../../apis/categories";
import { isListValid } from "~/shared/utils/validations";

const BroadcastRoomContext = createContext();

const BroadcastRoomsProvider = ({ children }) => {
  //* STATES
  const [categoriesList, setCategoriesList] = React.useState([]);
  const [roomsList, setRoomsList] = React.useState([]);
  const [search, setSearch] = React.useState("");
  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isSearching, setIsSearching] = React.useState(false);

  //* FUNCTIONS
  // Render List of categories
  React.useEffect(() => {
    const list = async () => {
      setIsSearching(true);
      await listBroadcastCategories({ search }).then(response => {
        let auxCategories = response.data;
        setCategoriesList(response.data);
        setIsLoaded(true);

        if (search !== "") {
          const sortedArray = auxCategories.sort((a, b) => {
            if (
              isListValid(a.broadcastRooms.data) &&
              !isListValid(b.broadcastRooms.data)
            ) {
              return -1;
            }
            if (
              isListValid(b.broadcastRooms.data) &&
              !isListValid(a.broadcastRooms.data)
            ) {
              return 1;
            }
            return 0;
          });

          auxCategories = sortedArray;
        }
      });

      setIsSearching(false);
    };

    list();
  }, [search]);

  // ? CATEGORY CRUD
  // function to create a category
  const handleCreateCategory = () => {
    listBroadcastCategories({
      search,
    }).then(response => {
      setCategoriesList(response.data);
    });
  };
  // Side effect when delete a category
  const handleDeleteCategory = categoryId => {
    const filteredCategories = categoriesList.filter(
      category => category.id !== categoryId
    );
    setCategoriesList(filteredCategories);
  };
  // Function to update a category
  const handleUpdateCategory = response => {
    const { order, name, id } = response.data;

    const indexOfCategoryUpdated = categoriesList.findIndex(categorie => {
      return categorie.id === id;
    });

    setCategoriesList(state => {
      if (indexOfCategoryUpdated !== -1) {
        state[indexOfCategoryUpdated].name = name;
        state[indexOfCategoryUpdated].order = order;
      }
      return [...state];
    });
  };
  // ? FINISH CATEGORY CRUD

  // ? CRUD BROADCASTROOM
  // Function to create a brodcastroom for categorie
  const handleCreateBroadcastRoom = categoryId => {
    openCreateBroadcastRoom({
      categoryId,
    });
  };
  // Function to delete a broadcastroom of categorie
  const handleDeleteBroadcastRoom = (broadcastRoomId, category) => {
    const indexOfCategory = categoriesList.findIndex(
      item => item.id === category.id
    );
    const broadcastRoomsFiltered = category.broadcastRooms.data.filter(
      broadcastRoom => {
        return broadcastRoom.id !== broadcastRoomId;
      }
    );
    deleteBroadcastRoom(broadcastRoomId).then(() => {
      setCategoriesList(state => {
        if (indexOfCategory !== -1) {
          state[indexOfCategory].broadcastRooms.data = broadcastRoomsFiltered;
        }
        return [...state];
      });
    });
  };
  // Function to edit a broadcastroom of categorie
  const handleEditBroadcastRoom = broadcastRoomId => {
    openEditBroadcastRoom(broadcastRoomId);
  };
  // ? FINISH CRUD BROADCASTROOM

  // ? DRAG AND DROP FUNCTIONS
  // Function to drag a broadcastroom to another category
  const handleDragItemToAnotherColumn = (
    draggedItem,
    destinationGroupId,
    categoryDestinationIndex,
    categorySourceIndex
  ) => {
    const broadcastRoomsOfDestination =
      categoriesList[categoryDestinationIndex].broadcastRooms.data;

    const broadcastRoomsOfSource = categoriesList[
      categorySourceIndex
    ].broadcastRooms.data.filter(broadcastRoom => {
      return broadcastRoom.id !== draggedItem.id;
    });

    updateCategoryBroadcastroom(draggedItem.id, destinationGroupId);
    setCategoriesList(state => {
      if (categoryDestinationIndex !== -1) {
        state[categorySourceIndex].broadcastRooms.data = broadcastRoomsOfSource;
        state[categoryDestinationIndex].broadcastRooms.data = [
          ...broadcastRoomsOfDestination,
          draggedItem,
        ];
      }
      return [...state];
    });
  };
  // Function to reorder the categories list
  const handleReorderCategories = newOrderCategories => {
    const reorderedList = newOrderCategories.map((field, index) => {
      return {
        ...field,
        id: Number(field.id),
        order: index,
      };
    });
    reorderCategory({ reorderedList }).then(setCategoriesList(reorderedList));
  };
  // Function to reorder the broadcastrooms list
  const handleMoveItemOfColumn = (newOrderCategory, categoryIndex) => {
    const reorderedArray = newOrderCategory.map((room, index) => {
      return {
        ...room,
        id: Number(room.id),
        order: index,
      };
    });

    const newOrderedBroadcastRooms = newOrderCategory.map((room, index) => {
      return {
        id: Number(room.id),
        order: index,
      };
    });

    reorderBroadcastRooms({ newOrderedBroadcastRooms }).then(() => {
      setCategoriesList(state => {
        if (categoryIndex !== -1) {
          state[categoryIndex].broadcastRooms.data = reorderedArray;
        }
        return [...state];
      });
    });
  };
  // ? FINISH DRAG AND DROP FUNCTIONS

  return (
    <BroadcastRoomContext.Provider
      value={{
        // STATES
        roomsList,
        categoriesList,
        isLoaded,
        isSearching,

        // FUNCTIONS
        setIsSearching,
        setRoomsList,
        setSearch,
        setCategoriesList,
        // CRUD BroadcastRoom
        handleCreateBroadcastRoom,
        handleEditBroadcastRoom,
        handleDeleteBroadcastRoom,
        // DRAG AND DROP FUNCIONS
        handleMoveItemOfColumn,
        handleDragItemToAnotherColumn,
        handleReorderCategories,
        // CRUD CATEGORIES
        handleCreateCategory,
        handleDeleteCategory,
        handleUpdateCategory,
      }}
    >
      {children}
    </BroadcastRoomContext.Provider>
  );
};

BroadcastRoomsProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useBroadcastRoomContext() {
  const context = useContext(BroadcastRoomContext);

  if (!context)
    throw new Error(
      "useBroadcastRoom must be used within BroadcastRoomsProvider"
    );

  return context;
}

export { BroadcastRoomsProvider, useBroadcastRoomContext };
