import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";

//* APIs
import {
  createCategory,
  updateCategory,
  deleteCategory,
} from "~/modules/signed/modules/cms/modules/admin/apis/categories";

//* CUSTOM IMPORTS
import { CATEGORY_TYPES, CATEGORY_LEVEL_TYPES } from "~/shared/utils/types";
import { AdminFormModal as FormModal } from "~/modules/signed/modules/cms/components/AdminFormModal";
import { useTranslation } from "~/shared/hooks/translation";
import { TranslateInput, Dropdown, DropdownFieldset } from "./styles";

const levels = [
  {
    id: 1,
    name: CATEGORY_LEVEL_TYPES.SILVER.name,
    value: CATEGORY_LEVEL_TYPES.SILVER.value,
  },
  {
    id: 2,
    name: CATEGORY_LEVEL_TYPES.GOLD.name,
    value: CATEGORY_LEVEL_TYPES.GOLD.value,
  },
  {
    id: 3,
    name: CATEGORY_LEVEL_TYPES.DIAMOND.name,
    value: CATEGORY_LEVEL_TYPES.DIAMOND.value,
  },
];

function CategoryModal({
  show,
  category,
  type,
  orderLimit,
  onCreate,
  onUpdate,
  onDelete,
  onClose,
}) {
  const { translate } = useTranslation();

  //* STATES
  const [orders, setOrders] = React.useState([]);
  const [isCreating, setisCreating] = React.useState(null);
  const [isUpdating, setIsUpdating] = React.useState(null);
  const [isDeleting, setIsDeleting] = React.useState(null);
  const [level, setLevel] = React.useState(
    category ? category.level : CATEGORY_LEVEL_TYPES.SILVER.value
  );

  //* FUNCTIONS
  /** Set order limit */
  React.useEffect(() => {
    const auxArray = [];

    for (let i = 0; i < orderLimit; i += 1) {
      auxArray.push({ id: i, name: String(i + 1) });
    }

    setOrders(auxArray);
  }, [orderLimit]);

  const handleSubmit = async data => {
    const { order } = data;

    const categoryData = {
      ...data,
      order: Number(order),
      level: type === CATEGORY_TYPES.MARKET_ROOM ? level : null,
    };

    if (category) {
      setIsUpdating("PENDING");
      await updateCategory({
        categoryData,
        type,
        categoryId: category.id,
      }).then(response => {
        onUpdate(response);
        setIsUpdating("FINISH");
      });
      onClose();
    } else {
      setisCreating("PENDING");
      await createCategory({ categoryData, type }).then(response => {
        onCreate(response);
        setisCreating("FINISH");
        onClose();
      });
    }
  };

  const handleDelete = async () => {
    setIsDeleting("PENDING");

    if (category) {
      await deleteCategory({ categoryId: category.id, type }).then(() => {
        onDelete(category.id);
        setIsDeleting("FINISH");
        onClose();
      });
    }
  };

  const getLevelConverted = l => {
    if (l === CATEGORY_LEVEL_TYPES.SILVER.value)
      return CATEGORY_LEVEL_TYPES.SILVER.name;

    if (l === CATEGORY_LEVEL_TYPES.GOLD.value)
      return CATEGORY_LEVEL_TYPES.GOLD.name;

    if (l === CATEGORY_LEVEL_TYPES.DIAMOND.value)
      return CATEGORY_LEVEL_TYPES.DIAMOND.name;

    return null;
  };

  return (
    <FormModal
      show={show}
      onClose={onClose}
      title={translate(
        category
          ? "entities.categories.editCategory"
          : "entities.categories.addCategories"
      )}
      initialData={category || {}}
      yupValidation={{
        name: Yup.string().required(
          translate("entities.categories.yupCategories")
        ),
      }}
      isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
      onSubmit={handleSubmit}
      enableDeleteButton={category}
      onDelete={handleDelete}
      isDeleting={isDeleting === "PENDING"}
      style={{ maxWidth: 750 }}
    >
      <TranslateInput
        name="name"
        title={translate("entities.categories.inputName")}
        placeholder={translate("entities.categories.inputNamePlaceholder")}
      />

      <DropdownFieldset>
        {type === CATEGORY_TYPES.MARKET_ROOM && (
          <Dropdown
            className="category-level"
            name="level"
            title={translate("entities.categories.DropdownField.title")}
            placeholder={translate(
              "entities.categories.DropdownField.placeholder"
            )}
            initialValue={
              category
                ? getLevelConverted(category.level)
                : CATEGORY_LEVEL_TYPES.SILVER.name
            }
            options={levels}
            onChange={value => setLevel(value)}
          />
        )}

        <Dropdown
          name="order"
          title={translate("entities.categories.DropdownCategory.title")}
          placeholder={translate(
            "entities.categories.DropdownCategory.placeholder"
          )}
          initialValue={category ? category.order : 1}
          options={orders}
        />
      </DropdownFieldset>
    </FormModal>
  );
}

CategoryModal.propTypes = {
  show: PropTypes.bool,
  category: PropTypes.object,
  onCreate: PropTypes.func,
  onUpdate: PropTypes.func,
  onDelete: PropTypes.func,
  onClose: PropTypes.func,
  type: PropTypes.string.isRequired,
  orderLimit: PropTypes.number,
};

CategoryModal.defaultProps = {
  show: false,
  category: null,
  onCreate: () => {},
  onUpdate: () => {},
  onDelete: () => {},
  onClose: () => {},
  orderLimit: 1,
};

export default CategoryModal;
