import styled from "styled-components";

//* CUSTOM IMPORTS
import { medias } from "~/shared/styles";
import { fadeIn } from "~/shared/styles/animations";
import { CellList } from "~/shared/components/molecules";
import { OutlinedButton } from "~/shared/components/atoms";
import Form from "./components/EventForm";

export const Container = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
`;

export const EventList = styled(CellList)`
  max-height: calc(100vh - 200px);
  max-width: 430px;
  width: 100%;

  @media (max-width: ${medias.wide}) {
    max-width: unset;
  }
`;

export const EventForm = styled(Form)`
  width: 100%;
  max-width: 480px;
  padding-left: 20px;
  animation: ${fadeIn} 0.3s ease;
  border-left: 1px solid ${props => props.theme.colors.shadow};

  @media (max-width: ${medias.wide}) {
    border-left: none;
    padding-left: 0px;
    margin-top: 16px;
    max-width: unset;
  }
`;

export const AddButton = styled(OutlinedButton)`
  padding: 16px;
  height: 74px;
  justify-content: flex-start;
  margin-bottom: 10px;
  width: 100%;

  #add-icon {
    height: 40px;
    width: 40px;
  }
`;
