import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

const AdminCMSContext = createContext(null);

const AdminCMSProvider = ({ children }) => {
  return (
    <AdminCMSContext.Provider value={{}}>{children}</AdminCMSContext.Provider>
  );
};

AdminCMSProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useAdminCMS() {
  const context = useContext(AdminCMSContext);

  if (!context)
    throw new Error("useAdminCMS must be used within AdminCMSProvider");

  return context;
}

export { AdminCMSProvider, useAdminCMS };
