import React from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listSpeakers,
  deleteSpeaker,
} from "~/modules/signed/modules/cms/modules/admin/store/speakers/actions";

//* CUSTOM IMPORTS
import { Icon } from "~/shared/components/atoms";
import { CheckList, CRUDModal } from "~/shared/components/organisms";
import {
  openCreateSpeakerModal,
  editEditSpeakerModal,
} from "~/shared/routes/navigation";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const speakerCheckListPropTypes = {
  selectedSpeakers: PropTypes.arrayOf(PropTypes.object).isRequired,
  setSelectedSpeakers: PropTypes.func.isRequired,
};

const SpeakerCheckList = ({
  selectedSpeakers,
  setSelectedSpeakers,
  ...rest
}) => {
  //* REDUCER
  const dispatch = useDispatch();
  const speakerReducer = useSelector(state => state.adminSpeakers.list);
  const { data: speakers, isLoaded, updateStatus } =
    speakerReducer && speakerReducer;

  //* STATES
  const [showDeleteModal, setShowDeleteModal] = React.useState(false);
  const [speakerData, setSpeakerData] = React.useState(null);

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listSpeakers());

    return () => {
      isMounted = false;
    };
  }, [dispatch]);

  function onAddButton() {
    openCreateSpeakerModal();
  }

  function handleSelectSpeaker(speakerId) {
    // Find the index of id
    const alreadySelected = selectedSpeakers.findIndex(
      item => item === speakerId
    );

    // If do not have de id inside array will return - 1
    if (alreadySelected >= 0) {
      // Receive all the array less the id
      const filteredTags = selectedSpeakers.filter(item => item !== speakerId);

      setSelectedSpeakers(filteredTags);
    } else {
      setSelectedSpeakers([...selectedSpeakers, speakerId]);
    }
  }

  const handleDeleteSpeaker = () => dispatch(deleteSpeaker(speakerData.id));

  React.useMemo(() => {
    if (updateStatus === "FINISH") {
      setShowDeleteModal(false);
      setSpeakerData(null);
    }
  }, [updateStatus]);

  return (
    <>
      <CheckList
        {...rest}
        data={speakers.map(speaker => ({
          ...speaker,
          avatar: speaker.picture,
          title: speaker.name,
          subTitle: dTranslate(speaker.role),
          isChecked: !!selectedSpeakers.includes(speaker.id),
        }))}
        onCheck={speaker => handleSelectSpeaker(speaker.id)}
        onClick={speaker => handleSelectSpeaker(speaker.id)}
        onEdit={speaker => editEditSpeakerModal(speaker)}
        onDelete={speaker => {
          setShowDeleteModal(true);
          setSpeakerData(speaker);
        }}
        isLoading={!isLoaded}
        iconedButton={{
          children: translate("entities.speakers.add"),
          onClick: onAddButton,
        }}
        empty={{
          icon: <Icon iconName="speakers" />,
          message: translate("entities.speakers.empty.message"),
          description: translate("entities.speakers.empty.description"),
        }}
        variant="avatared"
      />

      <CRUDModal
        type="delete"
        isLoading={updateStatus === "PENDING"}
        onClose={() => setShowDeleteModal(false)}
        onConfirm={handleDeleteSpeaker}
        show={showDeleteModal}
        entity={translate("entities.speakers.singular")}
        entityName={speakerData && speakerData.title}
      />
    </>
  );
};

SpeakerCheckList.propTypes = speakerCheckListPropTypes;

export { SpeakerCheckList, speakerCheckListPropTypes };
