import types from "./types";

// LIST
export function listBanners() {
  return {
    type: types.LIST.REQUEST,
  };
}
export function listBannersSuccessful({ banners }) {
  return {
    type: types.LIST.SUCCESSFUL,
    payload: { banners },
  };
}
export function clearBanners() {
  return {
    type: types.LIST.CLEAR,
  };
}
export function setBanners({ banners }) {
  return {
    type: types.LIST.SET,
    payload: { banners },
  };
}

// SHOW
export function showBanner({ bannerId }) {
  return {
    type: types.SHOW.REQUEST,
    payload: { bannerId },
  };
}
export function showBannerSuccessful({ banner }) {
  return {
    type: types.SHOW.SUCCESSFUL,
    payload: { banner },
  };
}
export function clearUser() {
  return {
    type: types.SHOW.CLEAR,
  };
}

// CREATE
export function createBanner({ bannerData }) {
  return {
    type: types.CREATE.REQUEST,
    payload: { bannerData },
  };
}
export function createBannerSuccessful({ banner }) {
  return {
    type: types.CREATE.SUCCESSFUL,
    payload: { banner },
  };
}
export function createBannerFailed() {
  return {
    type: types.CREATE.FAILED,
  };
}

// UPDATE
export function updateBanner({ bannerData, bannerId }) {
  return {
    type: types.UPDATE.REQUEST,
    payload: { bannerData, bannerId },
  };
}
export function updateBannerSuccessful({ banner }) {
  return {
    type: types.UPDATE.SUCCESSFUL,
    payload: { banner },
  };
}
export function updateBannerFailed() {
  return {
    type: types.UPDATE.FAILED,
  };
}

// DELETE
export function deleteBanner({ bannerId }) {
  return {
    type: types.DELETE.REQUEST,
    payload: { bannerId },
  };
}
export function deleteBannerSuccessful({ bannerId }) {
  return {
    type: types.DELETE.SUCCESSFUL,
    payload: { bannerId },
  };
}
export function deleteBannerFailed() {
  return {
    type: types.DELETE.FAILED,
  };
}

// EVENTS
export function clearEvents() {
  return {
    type: types.EVENTS.CLEAR,
  };
}

// REORDER
export function reorderBanners({ reorderedList }) {
  return {
    type: types.REORDER.REQUEST,
    payload: { reorderedList },
  };
}
export function reorderBannersSuccessful() {
  return {
    type: types.REORDER.SUCCESSFUL,
  };
}
export function reorderBannersFailed() {
  return {
    type: types.REORDER.FAILED,
  };
}
