import React from "react";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import { Box } from "~/shared/components/atoms";

//* CUSTOM IMPORTS
import CertificateModal from "./components/CertificateModal";
import { CertificateList } from "../../../../components/lists/CertificateList";
import { listCertificates } from "~/modules/signed/modules/client/store/certificates/actions";
import { useTranslation } from "~/shared/hooks/translation";

function MyCertificates() {
  //* REDUCER
  const dispatch = useDispatch();
  const { data: certificates, isLoaded } = useSelector(
    state => state.certificates.list
  );

  //* STATES
  const [certificateData, setCertificateData] = React.useState(null);

  //* FUNCTIONS
  const { translate } = useTranslation();

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true) dispatch(listCertificates());

    return () => {
      isMounted = false;
    };
  }, [dispatch]);

  return (
    <Box flex={1} paddingTop="20px">
      <CertificateList
        certificates={certificates.filter(certificate => certificate.available)}
        isLoaded={isLoaded}
        title={translate("pages.homeMyCertificates.available.title")}
        listStyle={{ marginBottom: 40 }}
        onCellClick={certificate => setCertificateData(certificate)}
      />

      <CertificateList
        certificates={certificates.filter(
          certificate => !certificate.available
        )}
        isLoaded={isLoaded}
        title={translate("pages.homeMyCertificates.unavailable.title")}
        onCellClick={value => setCertificateData(value)}
      />

      <CertificateModal
        show={!!certificateData}
        onClose={() => setCertificateData(null)}
        certificate={certificateData}
      />
    </Box>
  );
}

export default MyCertificates;
