import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Radio, radioPropTypes } from "../../atoms";
import { CellList } from "../CellList";

const radioGroupPropTypes = {
  /** An array of <b>Radio</b>, see all <b>Radio</b> props into <b>Atoms/Radio</b> */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      ...radioPropTypes,
    })
  ).isRequired,

  /** Define the name of <b>Radio</b>, if there is another <b>Radio</b> into DOM
   * with the same name, when you select one <b>Radio</b> the other ones will be unselected
   */
  name: PropTypes.string.isRequired,

  /** The id of the checked <b>Radio</b>, if is null no <b>Radio</b> will be checked  */
  checked: PropTypes.number,

  /** Callback that is triggered when click into an radio, return
   * the clicked radio data
   * (radio) => {}
   */
  onRadioClick: PropTypes.func,
};

const radioGroupDefaultProps = {
  onRadioClick: () => {},
  checked: null,
};

/** An Group of <b>Radio</b> components */
const RadioGroup = ({ data, name, onRadioClick, checked, ...rest }) => (
  <CellList
    isLoaded
    data={data}
    renderCell={radio => (
      <Radio
        {...radio}
        name={name}
        checked={checked === radio.id}
        onClick={() => onRadioClick(radio)}
      />
    )}
    {...rest}
  />
);

RadioGroup.propTypes = radioGroupPropTypes;

RadioGroup.defaultProps = radioGroupDefaultProps;

export { RadioGroup, radioGroupPropTypes, radioGroupDefaultProps };
