import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { badgePropTypes, badgeDefaultProps } from "..";

import { IconContainer, Badge } from "./styles";
import { setIcon } from "../../../utils/icons";

const iconPropTypes = {
  variant: PropTypes.oneOfType([
    "white",
    "opaque",
    "smoked",
    "primary",
    "accent",
    "error",
    "correct",
    "warning",
  ]),

  /** Define the icon size in "px", default is 20px */
  size: PropTypes.number,

  /** Define the icon color, if has no data the default color is <b>smoked</b>  */
  color: PropTypes.string,

  /** Defines if the 'something new' circle will appear */
  hasNew: PropTypes.bool,

  /** The Icon name to be rendered */
  iconName: PropTypes.string.isRequired,

  badgeOutlineColor: badgePropTypes.color,

  /** Add the background color */
  backgroundColor: PropTypes.string,
};

const iconDefaultProps = {
  variant: "opaque",
  size: 20,
  color: null,
  hasNew: false,
  badgeOutlineColor: badgeDefaultProps.outlineColor,
  backgroundColor: null,
};

/** This component has all icons of the application */
function Icon({
  variant,
  color,
  size,
  iconName,
  badgeOutlineColor,
  hasNew,
  backgroundColor,
  ...rest
}) {
  return (
    <IconContainer
      backgroundColor={backgroundColor}
      variant={variant}
      color={color}
      size={size}
      {...rest}
    >
      {hasNew && (
        <Badge
          outlineColor={badgeOutlineColor}
          variant="accent"
          size={size / 2.5}
        />
      )}

      {setIcon(iconName)}
    </IconContainer>
  );
}

Icon.propTypes = iconPropTypes;

Icon.defaultProps = iconDefaultProps;

export { Icon, iconPropTypes, iconDefaultProps };
