import React from "react";
import { useSelector, useDispatch } from "react-redux";
import PropTypes from "prop-types";

//* ACTIONS
import { setTagIds } from "~/modules/signed/modules/cms/modules/admin/store/partners/actions";

//* CUSTOM IMPORTS
import { TAG_TYPES } from "~/shared/utils/types";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";
import {
  ScrollContainer,
  LeftContainer,
  RigthContainer,
  TranslateInput,
  PlayerInput,
  PartnerAvatar,
  FileInput,
  MiniBanner,
  Tags,
} from "./styles";

function MarketRoomInfo({ partner, editMode }) {
  //* REDUCER
  const dispatch = useDispatch();
  const object = useSelector(state => state.adminPartners.object);
  const { tagIds } = object;
  const { translate } = useTranslation();

  //* FUNCTIONS
  function fillTagIds(tags) {
    dispatch(setTagIds(tags));
  }

  return (
    <ScrollContainer id="market-room-infos">
      <LeftContainer>
        <PartnerAvatar
          name="logo"
          initialValue={editMode ? partner.logo : null}
          labelMessage={
            <>
              <strong>
                {translate(
                  "entities.marketRooms.partnerAvatar.labelMessage.uploadImage"
                )}
              </strong>
              <h4>
                {translate(
                  "entities.marketRooms.partnerAvatar.labelMessage.acceptedFormats"
                )}{" "}
                (JPG | PNG)
              </h4>{" "}
            </>
          }
        />

        <TranslateInput
          title={translate("entities.marketRooms.inputSponsorName.title")}
          name="title"
          type="text"
          placeholder={translate(
            "entities.marketRooms.inputSponsorName.placeholder"
          )}
        />

        <TranslateInput
          title={translate("entities.marketRooms.inputSlogan.title")}
          name="sub_title"
          type="text"
          isTextArea
          placeholder={translate(
            "entities.marketRooms.inputSlogan.placeholder"
          )}
        />

        <TranslateInput
          title={translate("entities.marketRooms.inputDescription.title")}
          name="bio"
          type="text"
          isTextArea
          placeholder={translate(
            "entities.marketRooms.inputDescription.placeholder"
          )}
        />

        <Tags
          setSelectedTags={fillTagIds}
          selectedTags={tagIds}
          type={TAG_TYPES.PARTNERS}
        />
      </LeftContainer>

      <RigthContainer>
        <FileInput
          name="wallpaper"
          title={translate("entities.marketRooms.boothBanner.title")}
          initialValue={isDataValid(partner) ? partner.wallpaper : null}
          width={940}
          heigth={300}
        />

        <MiniBanner
          name="list_wallpaper"
          title={translate("entities.marketRooms.miniBanner.title")}
          initialValue={isDataValid(partner) ? partner.list_wallpaper : null}
        />

        <PlayerInput
          name="video_url"
          typeName="video_type"
          title={translate("entities.marketRooms.inputVideo.title")}
          placeholder={translate("entities.marketRooms.inputVideo.placeholder")}
          src={isDataValid(partner) ? partner.video_url : null}
        />
      </RigthContainer>
    </ScrollContainer>
  );
}

MarketRoomInfo.propTypes = {
  partner: PropTypes.object,
  editMode: PropTypes.bool,
};

MarketRoomInfo.defaultProps = {
  partner: null,
  editMode: false,
};

export default MarketRoomInfo;
