import React from "react";
import PropTypes from "prop-types";
import { IoIosArrowDown, IoIosArrowUp } from "react-icons/io";

//* CUSTOM IMPORTS
import { getFullDate, getCurrentDay, isSameDay } from "../../../utils/dates";

import DateSelectModal from "./DateSelectModal";
import { Trans, Typography } from "../../atoms";
import { Container, Menu } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const dateSelectPropTypes = {
  /** Callback that is triggered when user select an date. Return the stardDate
   * and the endDate of the selected date.
   *
   * Return Example: ({startDate, endDate}) => {}
   */
  onSelect: PropTypes.func,
  /** Object that contains startDate and endDate (DATE type) */
  initialValue: PropTypes.object,
};

const dateSelectDefaultProps = {
  onSelect: () => {},
  initialValue: null,
};

/** An Filter with dates, is commonly used inside the Dashboard */
const DateSelect = ({ onSelect, initialValue }) => {
  //* STATES
  const [isModalOpened, setIsModalOpened] = React.useState(false);
  const [isMenuOpened, setIsMenuOpened] = React.useState(false);
  const [interval, setInterval] = React.useState(
    initialValue || {
      startDate: null,
      endDate: null,
    }
  );

  //* FUNCTIONS
  const { translate } = useTranslation();

  const renderDateInterval = () => {
    const startDate = getFullDate(interval.startDate, true, true, true);
    const endDate = getFullDate(interval.endDate, true, true);

    const formmatedStartDate = startDate || "";
    let formmatedEndDate = endDate || "";

    //* If interval has endDate display him with an -
    if (interval.endDate) formmatedEndDate = ` - ${endDate}`;

    let result = `${formmatedStartDate} ${formmatedEndDate}`;

    //* If startDate and endDate is in the same day change interval label to Hoje
    if (
      isSameDay({
        dateLeft: interval.startDate,
        dateRight: interval.endDate,
      })
    )
      result = translate("common.today");

    return result;
  };

  const handleSelectDate = ({ startDate, endDate }) => {
    setInterval({ startDate, endDate });
    onSelect({ startDate, endDate });
  };

  const getToday = () => new Date();

  const dateOptions = [
    {
      id: 1,
      name: translate("components.dateSelect.option1"),
      function: () => {
        handleSelectDate({
          startDate: getCurrentDay({ inMidnigth: true }),
          endDate: getToday(),
        });
      },
    },
    {
      id: 2,
      name: translate("components.dateSelect.option2"),
      function: () => {
        handleSelectDate({
          startDate: getCurrentDay({ inMidnigth: true, subtractDays: 7 }),
          endDate: getToday(),
        });
      },
    },
    {
      id: 3,
      name: translate("components.dateSelect.option3"),
      function: () => {
        handleSelectDate({
          startDate: getCurrentDay({ inMidnigth: true, subtractDays: 30 }),
          endDate: getToday(),
        });
      },
    },
    {
      id: 4,
      name: translate("components.dateSelect.option4"),
      function: () => {
        handleSelectDate({
          startDate: getCurrentDay({ inMidnigth: true, subtractDays: 90 }),
          endDate: getToday(),
        });
      },
    },
    {
      id: 5,
      name: translate("components.dateSelect.option5"),
      withLine: true,
      function: () => setIsModalOpened(true),
    },
  ];

  return (
    <>
      <Menu
        onOpen={() => setIsMenuOpened(true)}
        onClose={() => setIsMenuOpened(false)}
        uppercaseItems
        button={
          <Container>
            <Typography className="bold tiny opaque uppercase">
              <Trans translationKey="components.dateSelect.filterTitle">
                Mostrando dados de
              </Trans>
            </Typography>
            <Typography className="date-filter">
              {renderDateInterval()}
            </Typography>

            {isMenuOpened ? <IoIosArrowUp /> : <IoIosArrowDown />}
          </Container>
        }
        fillItems={() => dateOptions}
      />

      <DateSelectModal
        show={isModalOpened}
        onClose={() => setIsModalOpened(false)}
        onSubmit={(startDate, endDate) => {
          setInterval({ startDate, endDate });
          onSelect({ startDate, endDate });
        }}
      />
    </>
  );
};

DateSelect.propTypes = dateSelectPropTypes;

DateSelect.defaultProps = dateSelectDefaultProps;

export { DateSelect, dateSelectPropTypes, dateSelectDefaultProps };
