import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container } from "./styles";

const hoverWrapperPropTypes = {
  children: PropTypes.oneOfType([PropTypes.array, PropTypes.element])
    .isRequired,

  /** If TRUE, remove the hover effect, default is "false" */
  disableHoverEffect: PropTypes.bool,

  /** Define the type of the effect when is hovered, default is "scale",
   *
   * <i>*Obs</i> The <b>"background"</b> variant uses <b>Button</b> component under the hood,
   * so see more in Atoms/Button
   */
  variant: PropTypes.oneOf(["scale", "background", "accentBackground"]),

  /** Define the border radius in px, default is <b>0</b> */
  borderRadius: PropTypes.number,

  /** If <b>true</b> this props will active the hover effect for this component. */
  hovered: PropTypes.bool,
};

const hoverWrapperDefaultProps = {
  disableHoverEffect: false,
  variant: "scale",
  borderRadius: 0,
  hovered: false,
};

/** A Wrapper thats add an hover effect to his children, this is the default hover effect of application */
function HoverWrapper({
  children,
  variant,
  borderRadius,
  disableHoverEffect,
  hovered,
  ...rest
}) {
  return (
    <Container
      hovered={hovered}
      variant={variant}
      disableHoverEffect={disableHoverEffect}
      borderRadius={borderRadius}
      {...rest}
    >
      {children}
    </Container>
  );
}

HoverWrapper.propTypes = hoverWrapperPropTypes;

HoverWrapper.defaultProps = hoverWrapperDefaultProps;

export { HoverWrapper, hoverWrapperPropTypes, hoverWrapperDefaultProps };
