import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import AvatarLoader from "./Loader";
import { Avatar, Input, Spinner } from "../../atoms";
import {
  Container,
  Title,
  Label,
  LabelMessage,
  LoaderContainer,
} from "./styles";
import { CropImageModal } from "../../organisms/CropImageModal";

const labeledAvatarinputPropTypes = {
  /** Name is required to reference the <b>LabeledAvatarInput</b> inside the <b>Form</b>  */
  name: PropTypes.string.isRequired,

  /** Render a title above the component */
  title: PropTypes.string,

  className: PropTypes.string,

  /** If has some data, will be the initial value for component.  */
  src: PropTypes.oneOfType([PropTypes.string, PropTypes.any]),

  /** If TRUE.removes the dependency on using a <b>Form</b> component as a Wrapper
   * and disable the anotation of component value
   */
  noRegistration: PropTypes.bool,

  /** Render an Loader instead of the component */
  isLoaded: PropTypes.bool,

  /** Render the label message  */
  labelMessage: PropTypes.any,

  /** Disable the component click */
  disabled: PropTypes.bool,

  /** Callback that is trigerred when user choose an file, return the file data */
  onChange: PropTypes.func,

  /** If TRUE, render an <b>Spinner</b> instead of the <b>src</b>, default is "false" */
  isLoading: PropTypes.bool,

  size: PropTypes.number,

  /** Control the image width in px, default is 300ox, his metric
   *  combine with heigth create the image aspect to the CropImage */
  width: PropTypes.number,

  /** Control the image height in px, default is 168px, This metric
   *  combine with width create the image aspect to the CropImage */
  height: PropTypes.number,
};

const labeledAvatarDefaultProps = {
  title: "",
  className: "",
  src: null,
  noRegistration: false,
  isLoaded: true,
  labelMessage: null,
  disabled: false,
  isLoading: false,
  onChange: () => {},
  size: 80,
  width: 80,
  height: 80,
};

/** LabeledAvatarInput is an file compoent,it uses <b>Form</b> component to anotate the current value, so he
 *  needs to be inside an <b>Form</b>. */
function LabeledAvatarInput({
  className,
  name,
  title,
  noRegistration,
  disabled,
  labelMessage,
  isLoaded,
  onChange,
  isLoading,
  src,
  size,
  height,
  width,
}) {
  // STATES
  const [cropPreview, setCropPreview] = React.useState(null);
  const [showCrop, setShowCrop] = React.useState(false);
  const heightAspect = height / width;

  //* FUNCTIONS
  const handleInputChange = e => {
    const binaryData = [];
    binaryData.push(e.target.files[0]);
    const file = window.URL.createObjectURL(
      new Blob(binaryData, { type: "application/zip" })
    );

    setCropPreview(file);
    setShowCrop(true);
  };

  const handleUploadCroppedImage = file => {
    const data = new FormData();
    data.append("file", file);
    onChange(data);
  };

  return isLoaded ? (
    <Container className={className}>
      {title && <Title>{title}</Title>}

      <Label htmlFor={name} disabled={disabled}>
        {isLoading ? (
          <LoaderContainer className="file-input-image-preview">
            <Spinner color="#fff" />
          </LoaderContainer>
        ) : (
          <Avatar size={size} className="file-input-image-preview" src={src} />
        )}

        {labelMessage && <LabelMessage>{labelMessage}</LabelMessage>}

        <input
          className="file-input"
          type="file"
          id={name}
          accept="image/*"
          onChange={e => handleInputChange(e)}
        />
      </Label>

      {!noRegistration && (
        <Input className="file-input" name={name} type="text" value={src} />
      )}

      <CropImageModal
        show={showCrop}
        onClose={() => setShowCrop(false)}
        src={cropPreview}
        widthAspect={1}
        heightAspect={heightAspect}
        fileName="file"
        onSave={handleUploadCroppedImage}
      />
    </Container>
  ) : (
    <AvatarLoader className="labeled-avatar-loader" />
  );
}

LabeledAvatarInput.propTypes = labeledAvatarinputPropTypes;

LabeledAvatarInput.defaultProps = labeledAvatarDefaultProps;

export {
  LabeledAvatarInput,
  labeledAvatarinputPropTypes,
  labeledAvatarDefaultProps,
};
