import api from "~/shared/services/api";

export const createPosterRate = ({ rate, posterId }) =>
  Promise.resolve(
    api.post(`/rateroom`, { ref_id: posterId, rate, type: "poster" })
  );

export const showPosterRate = ({ posterId }) =>
  Promise.resolve(api.get(`/rateroom/poster`, { ref_id: posterId }));
