import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { useWindow } from "../../../hooks/window";
import { isListValid } from "../../../utils/validations";
import { Carrousal } from "../../molecules";
import { Container, CarrouselLoader } from "./styles";

const bannersPropTypes = {
  isLoaded: PropTypes.bool,
  onClick: PropTypes.func,
  banners: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.id,
      image: PropTypes.string,
      url: PropTypes.url,
    })
  ).isRequired,
};

const bannersDefaultProps = {
  onClick: () => {},
  isLoaded: false,
};

/** The Banners control witch Carrousal will be displayed, based on
 * window width.
 */
function Banners({ isLoaded, banners, onClick }) {
  const { isTablet, isMobile } = useWindow();

  return isLoaded ? (
    isListValid(banners) && (
      <Container id="banners">
        {isTablet || isMobile ? (
          <Carrousal className="carrousel" slides={banners} onClick={onClick} />
        ) : (
          <Carrousal
            variant="3d"
            className="carrousel-3d"
            slides={banners}
            onClick={onClick}
          />
        )}
      </Container>
    )
  ) : (
    <CarrouselLoader
      style={{ width: "100%", maxWidth: 950, margin: "0 auto" }}
    />
  );
}

Banners.propTypes = bannersPropTypes;

Banners.defaultProps = bannersDefaultProps;

export { Banners, bannersPropTypes, bannersDefaultProps };
