import api from "~/shared/services/api";

export const getOnlineUsers = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/access_log/count", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );

export const getAccessLogReportCount = ({ startDate, endDate }) =>
  Promise.resolve(
    api.get("/admin/access_log/report/count", {
      params: {
        start_date: startDate,
        end_date: endDate,
      },
    })
  );
