import React from "react";
import PropTypes from "prop-types";
import { MdKeyboardArrowRight, MdKeyboardArrowLeft } from "react-icons/md";

import {
  Container,
  Slide,
  LeftOverlay,
  RightOverlay,
  PrevButton,
  NextButton,
} from "./styles";

const cellSliderPropTypes = {
  /** Controls the slider to be in the center by index */
  checked: PropTypes.number.isRequired,

  /** Add an gap in 'px' between cells */
  gap: PropTypes.number,

  /** An Array of objects to be rendered in <b>CellSlider</b> */
  data: PropTypes.arrayOf(PropTypes.object).isRequired,

  /** An Function to render the Cells of the <b>CellSlider</b> */
  renderCell: PropTypes.func.isRequired,

  /** An Callback that is triggered when user change the slide, return 2 params
   * the current item data that will be in the center, and the item index
   */
  onChange: PropTypes.func,
};

const cellSliderDefaultProps = {
  gap: 0,
  onChange: () => {},
};

/** CellSlider is an list of Cells, that works similar with an Carrousal  */
function CellSlider({ data, checked, renderCell, gap, onChange, ...rest }) {
  //* FUNCTINOS
  const hasOnlyOneSlide = data.length === 1;

  const isLastSlide = index => index === data.length - 1;

  const isFirstSlide = index => index === 0;

  const handleChange = index => {
    onChange(index, data[index]);
  };

  const isNextSlide = index => {
    //* *If has only one slide next slide do not exists */
    if (hasOnlyOneSlide) return false;

    //* *If the checked slide is the last one, the next slide is the first one */
    if (isLastSlide(checked) && isFirstSlide(index)) return true;

    if (index === checked + 1) return true;

    return false;
  };

  const isPrevSlide = index => {
    //* *If has only one slide prev slide do not exists */
    if (hasOnlyOneSlide) return false;

    //* *If the checked slide is the first one, the prev slide is the last one */
    if (isFirstSlide(checked) && isLastSlide(index)) return true;

    if (index === checked - 1) return true;

    return false;
  };

  const handlePrevButtonClick = () => {
    if (isFirstSlide(checked)) {
      handleChange(data.length - 1);
      return;
    }

    handleChange(checked - 1);
  };

  const handleNextButtonClick = () => {
    if (isLastSlide(checked)) {
      handleChange(0);
      return;
    }

    handleChange(checked + 1);
  };

  return (
    <Container checked={checked} {...rest}>
      {!hasOnlyOneSlide && (
        <PrevButton onClick={handlePrevButtonClick}>
          <MdKeyboardArrowLeft />
        </PrevButton>
      )}

      {!hasOnlyOneSlide && <LeftOverlay />}

      {data.map((obj, index) => (
        <Slide
          gap={gap}
          isPrevOne={isPrevSlide(index)}
          isNextOne={isNextSlide(index)}
          checked={checked === index}
        >
          {renderCell(obj, index)}
        </Slide>
      ))}

      {!hasOnlyOneSlide && (
        <NextButton onClick={handleNextButtonClick}>
          <MdKeyboardArrowRight />
        </NextButton>
      )}

      {!hasOnlyOneSlide && <RightOverlay />}
    </Container>
  );
}

CellSlider.propTypes = cellSliderPropTypes;

CellSlider.defaultProps = cellSliderDefaultProps;

export { CellSlider, cellSliderDefaultProps, cellSliderPropTypes };
