import React from "react";
import { useParams } from "react-router-dom";

//* CUSTOM IMPORTS
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import FolderMaterialsList from "../FolderMaterialsList";
import { isDataValid } from "~/shared/utils/validations";
import { BreadCrumbs } from "~/shared/components/molecules";
import { useDownloadMaterial } from "../../../hooks";
import { Container } from "./styles";

function FolderDetails() {
  // GET FUNCTION INSIDE ADMIN LAYOUT
  const { folderId } = useParams();
  const {
    showFolder,
    selectedFolderPath,
    setMaterialFolder,
    folderPaths,
    renderFolderPaths,
    folderTitle,
    setFolderTitle,
  } = useDownloadMaterial();
  const { dTranslate } = useDynamicTranslation();

  // FUNCTIONS
  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderId)) {
      showFolder(folderId);
    }

    return () => {
      isMounted = false;
      setMaterialFolder(null);
      setFolderTitle(null);
    };
  }, [folderId, setFolderTitle, setMaterialFolder, showFolder]);

  React.useEffect(() => {
    let isMounted = true;

    if (isMounted === true && isDataValid(folderTitle)) {
      renderFolderPaths(folderId, folderTitle);
    }

    return () => {
      isMounted = false;
      setFolderTitle(null);
    };
  }, [folderId, folderTitle, renderFolderPaths, setFolderTitle]);

  return (
    <Container>
      <BreadCrumbs
        data={folderPaths.map(path => ({
          ...path,
          name: dTranslate(path.name),
        }))}
        selectedCrumb={selectedFolderPath}
      />

      <FolderMaterialsList />
    </Container>
  );
}

export default FolderDetails;
