import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Box } from "../../atoms";
import { FooterButtons, DialogModal as CancelModal } from "../../molecules";
import { CRUDModal } from "../CRUDModal";
import { useTranslation } from "../../../hooks/translation";

const propTypes = {
  className: PropTypes.string,

  style: PropTypes.object,

  /** The current object entity,  is used to create the description and title of <b>CRUDModal</b> */
  entity: PropTypes.string.isRequired,

  /** The current object name,  is used to create the description and title of <b>CRUDModal</b> */
  entityName: PropTypes.string.isRequired,

  /** Used to trigger an submit in the form with the correspondent ID */
  formId: PropTypes.string,

  /** Callback that is triggered when the confirm button of the ConfirmModal is clicked */
  onConfirm: PropTypes.func,

  /** Callback that is triggered when the cancel button of the CancelModal is clicked */
  onCancel: PropTypes.func,

  /** Callback that is triggered when the confirm Button is clicked */
  onClick: PropTypes.func,

  /** Render an spinner inside the confirm button */
  isLoading: PropTypes.bool,

  /** Define the type of the confirm button, Default is "submit" */
  type: PropTypes.string,

  /** If TRUE display an <b>CRUDModal</b> */
  showModal: PropTypes.bool,

  /** Define the type of the Modal, this type changes the title and description
   * inside the modal, Default is "create"
   */
  modalType: PropTypes.oneOf(["create", "updtate", "delete"]),

  /** If TRUE hide Cancel Button */
  disableCancelButton: PropTypes.bool,
};

const defaultProps = {
  className: null,
  style: null,
  formId: null,

  onConfirm: () => {},
  onCancel: () => {},
  onClick: () => {},

  isLoading: false,
  showModal: false,
  disableCancelButton: false,

  type: "submit",
  modalType: "create",
};

/** Footer is an CancelButton with an ConfirmButton, and has an CRUDModal to handle
 * CRUD operations
 */
function Footer({
  className,
  style,
  onCancel,
  onConfirm,
  isLoading,
  formId,
  type,
  modalType,
  showModal,
  onClick,
  entity,
  entityName,
  disableCancelButton,
  ...rest
}) {
  // STATES
  const [showCancelModal, setShowCancelModal] = React.useState(false);
  const [showConfirmModal, setConfirmModal] = React.useState(
    showModal || false
  );

  // FUNCTIONS
  const { translate } = useTranslation();

  return (
    <Box className={className} style={style}>
      <FooterButtons
        {...rest}
        disableCancelButton={disableCancelButton}
        formId={formId}
        type={type}
        isLoading={isLoading}
        onClick={() => {
          setConfirmModal(true);
          onClick();
        }}
        onCancel={() => setShowCancelModal(true)}
      />

      <CancelModal
        show={showCancelModal}
        onClose={() => setShowCancelModal(false)}
        onCancel={() => setShowCancelModal(false)}
        onConfirm={onCancel}
        title={translate("components.footer.cancelModal.title")}
        description={
          <>
            {translate("components.footer.cancelModal.description.part1")}
            <strong>
              {" "}
              {translate("components.footer.cancelModal.description.part2")}
            </strong>
            {translate("components.footer.cancelModal.description.part3")}
          </>
        }
      />

      <CRUDModal
        entity={entity}
        entityName={entityName}
        type={modalType}
        show={showConfirmModal && showModal}
        isLoading={isLoading}
        onClose={() => setConfirmModal(false)}
        onConfirm={onConfirm}
      />
    </Box>
  );
}

Footer.propTypes = propTypes;

Footer.defaultProps = defaultProps;

export default Footer;
