import produce from "immer";

import { isDataValid, isListValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: {},
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  object: {
    data: {},
    isLoaded: false,
  },

  activities: {
    data: [],
    isLoaded: false,
    isPaging: false,
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.REQUEST: {
        draft.list.isLoaded = false;

        break;
      }
      case types.LIST.SUCCESSFUL: {
        const { users } = action.payload;

        if (isDataValid(users)) draft.list.data = users;

        draft.list.isLoaded = true;

        break;
      }
      case types.LIST.CLEAR: {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }

      // SHOW
      case types.SHOW.SUCCESSFUL: {
        const { user } = action.payload;

        if (isDataValid(user)) draft.object.data = user;

        draft.object.isLoaded = true;

        break;
      }

      case types.SHOW.CLEAR: {
        draft.object.data = [];
        draft.object.isLoaded = false;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { user } = action.payload;

        if (isDataValid(user)) {
          if (isDataValid(draft.list.data)) {
            if (isListValid(draft.list.data.data))
              draft.list.data.data = [user, ...draft.list.data.data];

            draft.list.isCreating = "FINISH";
          }
        }

        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        draft.list.isUpdating = "PENDING";
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { user } = action.payload;

        if (isDataValid(draft.list.data)) {
          if (isListValid(draft.list.data.data)) {
            const users = draft.list.data.data;
            const userIndex = users.findIndex(u => u.id === Number(user.id));

            users[userIndex] = user;

            draft.list.data.data = users;
          }
        }

        draft.list.isUpdating = "FINISH";
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { userId } = action.payload;

        if (isDataValid(draft.list.data)) {
          if (isListValid(draft.list.data.data)) {
            const filteredArray = draft.list.data.data.filter(
              obj => obj.id !== Number(userId)
            );

            draft.list.data.data = filteredArray;
          }
        }

        draft.list.isDeleting = "FINISH";

        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // FORM
      case types.FORM.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;

        break;
      }

      // ACTIVITIES
      case types.ACTIVITIES.REQUEST: {
        const { page } = action.payload;

        if (page > 1) draft.activities.isPaging = true;

        break;
      }

      case types.ACTIVITIES.SUCCESSFUL: {
        const { activities, page } = action.payload;

        if (isDataValid(activities)) {
          if (page === 1) {
            draft.activities.data = activities;
          } else {
            draft.activities.data.data = [
              ...draft.activities.data.data,
              ...activities.data,
            ];
          }
        }

        draft.activities.isLoaded = true;
        draft.activities.isPaging = false;

        break;
      }
      case types.ACTIVITIES.CLEAR: {
        draft.activities.data = {};
        draft.activities.isLoaded = false;
        draft.activities.isPaging = false;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isLoaded = false;

        draft.object.data = {};
        draft.object.tagIds = [];
        draft.object.isLoaded = false;

        draft.activities.data = {};
        draft.activities.isLoaded = false;
        draft.activities.isPaging = false;

        break;
      }
      default:
    }
  });
}
