/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback } from "react";
import { useLocation, useRouteMatch, Route } from "react-router-dom";

//* CUSTOM IMPORTS
import i18n from "~/shared/translation/i18n";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { ImportUsersProvider, useImportUsers } from "./hooks";

import { adminPaths } from "~/shared/routes/paths";
import { SwipeTabs } from "~/shared/components/molecules";

import {
  openAdminUsers,
  openAdminUserImport,
  openAdminUserImportMatch,
  openAdminUserImportReview,
  openAdminUserImportSettings,
} from "~/shared/routes/navigation";

//* TABS
import ImportTab from "./pages/Import";
import SettingsTab from "./pages/Settings";
import MatchInformationTab from "./pages/MatchInformation";
import ReviewTab from "./pages/Review";
import ImportUsersStatus from "./pages/Status";

const {
  index: importPath,
  match: matchPath,
  settings: settingsPath,
  review: reviewPath,
  status: statusPath,
} = adminPaths.users.import;

const TABS = [
  {
    tabIndex: 0,
    path: importPath,
    name: i18n.t("entities.users.import"),
    function: openAdminUserImport,
  },
  {
    tabIndex: 1,
    path: matchPath,
    name: i18n.t("common.matchColumns"),
    function: openAdminUserImportMatch,
  },
  {
    tabIndex: 2,
    path: settingsPath,
    name: i18n.t("entities.users.newUserSettings"),
    function: openAdminUserImportSettings,
  },
  {
    tabIndex: 3,
    path: reviewPath,
    name: i18n.t("common.reviewAndImport"),
    function: openAdminUserImportReview,
  },
  {
    tabIndex: 4,
    path: statusPath,
    name: i18n.t("common.importStatus"),
  },
];

const Tabs = () => {
  const { path } = useRouteMatch();
  const location = useLocation();
  const {
    breadCrumbs,
    setBreadCrumbs,
    setFooterButtons,
    scrollToTop,
  } = useCMS();
  const { translate } = useTranslation();
  const { tabIndex, setTabIndex, sheetData } = useImportUsers();

  //* STATES
  const [isCleaning, setIsCleaning] = React.useState(false);
  const [persistBreadCrumbs, setPersistBreadCrumbs] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    return () => {
      setFooterButtons(null);
    };
  }, []);

  const resetBreadCrumbs = useCallback(
    () =>
      setBreadCrumbs([
        {
          id: 1,
          name: translate("entities.users.plural"),
          function: openAdminUsers,
        },
        TABS[0],
      ]),
    []
  );

  const handleCleaningData = () => {
    setIsCleaning(true);

    setTimeout(() => setIsCleaning(false), 1);
  };

  React.useEffect(() => {
    const formmatedBreadCrumbs = [];

    //* *Add users breadcrumb if do not have one in breadcrumbs */
    if (!breadCrumbs.some(b => b.id === 1))
      formmatedBreadCrumbs.push({
        id: 1,
        name: translate("entities.users.plural"),
        function: openAdminUsers,
      });

    const tab = TABS.find(
      t => t.path === String(location.pathname).replace(/\/\d+/g, "")
    );

    if (tab) {
      if (tab.path === statusPath) {
        setPersistBreadCrumbs(breadCrumbs);
      } else {
        const breadCrumb = {
          id: tab.tabIndex + 1,
          name: tab.name,
          function: tab.function,
        };

        if (persistBreadCrumbs.length > 0) {
          if (sheetData.length > 0) {
            setBreadCrumbs(persistBreadCrumbs);
          } else {
            resetBreadCrumbs();
          }

          setPersistBreadCrumbs([]);
        } else if (!breadCrumbs.some(b => b.name === breadCrumb.name)) {
          formmatedBreadCrumbs.push(breadCrumb);

          //* *If sheet data has no data and user is not in the import page redirect */
          if (
            sheetData.length <= 0 &&
            location.pathname !== "/admin/users/import"
          ) {
            formmatedBreadCrumbs.pop();

            openAdminUserImport();
          }

          setBreadCrumbs([...breadCrumbs, ...formmatedBreadCrumbs]);
        }
      }

      //* *Change Tab in Swipe Views */
      setTabIndex(tab.tabIndex);

      scrollToTop();
    }
  }, [location.pathname, setBreadCrumbs]);

  return (
    <SwipeTabs
      disableTabs
      controlled
      index={tabIndex}
      childrenStyle={{ padding: 4 }}
      onChange={value => setTabIndex(value)}
    >
      <ImportTab onReset={resetBreadCrumbs} isCleaning={isCleaning} />

      {!isCleaning && <MatchInformationTab />}

      {!isCleaning && <SettingsTab />}

      {!isCleaning && <ReviewTab onImport={handleCleaningData} />}

      {tabIndex === 4 && (
        <Route
          path={`${path}/status/:importId`}
          component={ImportUsersStatus}
        />
      )}
    </SwipeTabs>
  );
};

const ImportUsers = () => {
  return (
    <ImportUsersProvider>
      <Tabs />
    </ImportUsersProvider>
  );
};

export default ImportUsers;
