import Spinner from "react-bootstrap/Spinner";
import { Card as CustomCard, Box } from "../../atoms";

export const Container = styled.fieldset`
  width: 100%;
`;

export const Card = styled(CustomCard)`
  width: 100%;
  border-radius: 4px;
  padding: 5px;
  background: ${props => props.theme.colors.surface};
  box-shadow: 0 2px 5px ${props => props.theme.colors.shadow};
`;

export const Title = styled.h4`
  margin: 0 0 10px 0;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: ${props => props.theme.colors.normal};
  width: 100%;
  text-align: left;
`;

export const ImagePreview = styled.div`
  background: ${props => props.theme.colors.shadow};
  width: 100%;
  height: 0;
  padding-top: ${props => props.heightAspect * 100}%;
  border-radius: 4px;
  position: relative;

  ${props =>
    props.src &&
    css`
      background: url(${props.src}) center no-repeat;
      background-size: cover;
    `}
`;

export const ImageAspect = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  font-weight: bold;
  color: ${props => props.theme.colors.normal};
`;

export const LoaderContainer = styled.div`
  width: 100%;
  height: 160px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${props => props.theme.colors.background};
`;

export const Loader = styled(Spinner)`
  color: ${props => props.theme.colors.accent};
`;

export const FileInputArea = styled(Box)`
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-top: 5px;
  padding: 5px 5px 5px 10px;

  width: 100%;
  height: 50px;

  border: 1px solid ${props => props.theme.colors.shadow};
  border-radius: 8px;

  strong {
    padding: 0 10px;
    flex: 1;
    font-size: 12px;
    color: ${props => props.theme.colors.opaque};
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .file-input {
    display: none;
  }
`;
