/* eslint-disable no-console */
import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listPartnersSuccessful,
  showPartnerSuccessful,
  craetePartnersFailed,
  craetePartnersSuccessful,
  updatePartnersSuccessful,
  updatePartnersFailed,
  deletePartnersSuccessful,
  deletePartnersFailed,
  clearPartnerForm,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, `/admin/partner`);

    if (validResponseStatus(response.status)) {
      const partners = response.data;

      yield put(listPartnersSuccessful(partners));
    }
  } catch (err) {
    console.log(err);
  }
}

// SHOW
export function* show({ payload }) {
  try {
    const { partnerId } = payload;

    const response = yield call(api.get, `/admin/partner/${partnerId}`);

    if (validResponseStatus(response.status)) {
      const partners = response.data;

      yield put(showPartnerSuccessful(partners));
    }
  } catch (err) {
    console.log(err);
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { partner, marketRoomId } = payload;

    const response = yield call(api.post, "/admin/partner", {
      ...partner,
      category_id: marketRoomId,
    });
    if (validResponseStatus(response.status)) {
      const newPartner = response.data;

      yield put(craetePartnersSuccessful(newPartner));
    }
    yield put(clearPartnerForm());
  } catch (err) {
    yield put(craetePartnersFailed());
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { partnerId, partner } = payload;

    const response = yield call(
      api.put,
      `/admin/partner/${partnerId}`,
      partner
    );
    if (validResponseStatus(response.status)) {
      const updatedPartner = response.data;

      yield put(updatePartnersSuccessful(updatedPartner));
    }
    yield put(clearPartnerForm());
  } catch (err) {
    yield put(updatePartnersFailed());
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { partnerId, marketRoomId } = payload;

    const response = yield call(api.delete, `/admin/partner/${partnerId}`);

    if (validResponseStatus(response.status)) {
      yield put(deletePartnersSuccessful(partnerId, marketRoomId));
    }
    yield put(clearPartnerForm());
  } catch (err) {
    yield put(deletePartnersFailed());
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.SHOW.REQUEST, show),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
]);
