import React, { useState, useCallback, useRef, useEffect } from "react";
import PropTypes from "prop-types";

import "react-image-crop/dist/ReactCrop.css";

// CUSTOM IMPORTS
import { blobToFile } from "../../../utils/images";
import { Modal, ReactCrop, Canvas, Footer } from "./styles";

const cropImageModalPropTypes = {
  /** Modal visibility  */
  show: PropTypes.bool,

  /** Callback that is triggered when user clicks in <b>CloseButton</b>. Return is <b>void</b> */
  onClose: PropTypes.func,

  /** Callback that is triggered when user saves the image, return the new image
   * cropped.
   *
   * Example: (canvas) => {}
   */
  onSave: PropTypes.func.isRequired,

  /** The width aspect ratio in number */
  widthAspect: PropTypes.number,

  /** The height aspect ratio in number */
  heightAspect: PropTypes.number,

  /** The name of the file */
  fileName: PropTypes.string,

  /** File source to be cropped */
  src: PropTypes.any.isRequired,
};

const cropImageModalDefaltProps = {
  show: false,
  onClose: () => {},
  widthAspect: 1,
  heightAspect: 0.56,
  fileName: "cropImage",
};

/** CropImageModal will enable the user to cut the image as his wish. */
function CropImageModal({
  show,
  onClose,
  onSave,
  src,
  widthAspect,
  heightAspect,
  fileName,
  ...rest
}) {
  // REFS
  const imgRef = useRef(null);
  const cropRef = useRef(null);
  const previewCanvasRef = useRef(null);

  // Increase pixel density for crop preview quality on retina screens.
  const pixelRatio = 2;

  // STATES
  const [crop, setCrop] = useState({
    unit: "%",
    width: 80,
    aspect: widthAspect / heightAspect || 1 / 0.56,
  });
  const [completedCrop, setCompletedCrop] = useState(null);

  // FUNCTIONS
  const handleOnClose = () => {
    onClose();
  };

  // We resize the canvas down when saving on retina devices otherwise the image
  // will be double or triple the preview size.
  function getResizedCanvas() {
    const scaleX = imgRef.current.naturalWidth / imgRef.current.width;
    const scaleY = imgRef.current.naturalHeight / imgRef.current.height;
    const tmpCanvas = document.createElement("canvas");
    tmpCanvas.width = Math.ceil(crop.width * scaleX);
    tmpCanvas.height = Math.ceil(crop.height * scaleY);

    const ctx = tmpCanvas.getContext("2d");
    const image = imgRef.current;
    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );

    return tmpCanvas;
  }

  const handleOnSave = canvas => {
    onSave(canvas);
    onClose();
  };

  const onLoad = useCallback(img => {
    imgRef.current = img;
  }, []);

  function uploadCroppedFile(previewCanvas, c) {
    if (!c || !previewCanvas) {
      return;
    }

    const canvas = getResizedCanvas();

    canvas.toBlob(blob => {
      handleOnSave(blobToFile(blob, fileName));
    }, "image/png");
  }

  useEffect(() => {
    if (!completedCrop || !previewCanvasRef.current || !imgRef.current) {
      return;
    }

    const image = imgRef.current;
    const canvas = previewCanvasRef.current;
    // eslint-disable-next-line no-shadow
    const crop = completedCrop;

    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext("2d");

    canvas.width = crop.width * pixelRatio;
    canvas.height = crop.height * pixelRatio;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = "high";

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );
  }, [completedCrop, pixelRatio]);

  return (
    <Modal
      {...rest}
      title="Cortar Imagem"
      id="crop-image-modal"
      show={show}
      onClose={handleOnClose}
      withScroll
    >
      <ReactCrop
        ref={cropRef}
        src={src}
        crop={crop}
        onChange={c => setCrop(c)}
        onImageLoaded={onLoad}
        onComplete={c => setCompletedCrop(c)}
      />

      <Canvas ref={previewCanvasRef} />

      <Footer
        onCancel={handleOnClose}
        onClick={() =>
          uploadCroppedFile(previewCanvasRef.current, completedCrop)
        }
      />
    </Modal>
  );
}

CropImageModal.propTypes = cropImageModalPropTypes;

CropImageModal.defaultProps = cropImageModalDefaltProps;

export { CropImageModal, cropImageModalPropTypes, cropImageModalDefaltProps };
