import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Button as ButtonC, Box as BoxC } from "../../atoms";

export const Box = styled(BoxC)`
  display: flex;

  ${props =>
    props.animated &&
    css`
      flex-grow: 10;
      flex-shrink: 0;
      flex-basis: auto;
    `}
`;

export const Button = styled(ButtonC)`
  ${props =>
    props.animated &&
    css`
      flex-grow: ${props.isSelected ? 1 : 0.01};
      width: ${props.isSelected ? "200px" : "62px"};
      white-space: nowrap;
      max-width: 200px;

      transition: all 0.5s ease-out;

      .MuiButton-label {
        p {
          opacity: ${props.isSelected ? 1 : 0};
          transition: opacity 0.5s ease-in;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    `}
`;
