import React from "react";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { useTranslation } from "~/shared/hooks/translation";

//* CUSTOM IMPORTS
import { getFullDate, getFullHours } from "~/shared/utils/dates";

const EXTRACT_TYPES = {
  PARTNER_SOCIAL: "partner_social",
  PARTNER_EXTRA_BUTTON: "partner_extra_button",
  BOOKMARK_PARTNERS: "boorkmark_partners",
  BOOKMARK_ROOMS: "boorkmark_rooms",
  BOOKMARK_SCHEDULES: "boorkmark_schedules",
  BOOKMARK_SPEAKERS: "boorkmark_speakers",
  RATE_ROOM: "rate_room",
  RATE_SCHEDULE: "rate_schedule",
  SENDED_QUESTION: "sended_question",
  SENDED_MESSAGE: "sended_message",
  SENDED_INDIVIDUAL_MESSAGE: "sended_individual_message",
  REPLIED_MESSAGE: "replied_message",
  REACTION_MESSAGE: "reaction_message",
  REACTION_BROADCAST_ROOM: "reaction_broadcast_room",
  ACCESS_LOG_BROADCAST: "access_log_broadcast",
  ACCESS_LOG_MARKET_ROOM: "access_log_market_room",
  ACCESS_LOG_FIRST_ACCESS: "access_log_first_access",
  ADMIN_UPDATE: "admin_update",
  ADMIN_RESET: "admin_reset",
  FRIEND_CONNECTION: "friend_connection",
};

const {
  PARTNER_SOCIAL,
  PARTNER_EXTRA_BUTTON,
  BOOKMARK_PARTNERS,
  BOOKMARK_ROOMS,
  BOOKMARK_SCHEDULES,
  BOOKMARK_SPEAKERS,
  RATE_ROOM,
  RATE_SCHEDULE,
  SENDED_QUESTION,
  SENDED_MESSAGE,
  SENDED_INDIVIDUAL_MESSAGE,
  REPLIED_MESSAGE,
  REACTION_MESSAGE,
  REACTION_BROADCAST_ROOM,
  ACCESS_LOG_BROADCAST,
  ACCESS_LOG_MARKET_ROOM,
  ACCESS_LOG_FIRST_ACCESS,
  ADMIN_UPDATE,
  ADMIN_RESET,
  FRIEND_CONNECTION,
} = EXTRACT_TYPES;

/** this function deal with information and color each log will be rendered,
 * based on the type of each log.
 */
const renderExtractPoint = (extractPoint, translate, dTranslate) => {
  const {
    id,
    transitional_points: points,
    created_at: createdAt,
    type,
    title,
  } = extractPoint;

  const formmatedPoints = `${points > 0 ? "+" : ""}${points}pts`;

  const defaultPoint = {
    id,
    points: formmatedPoints,
    date: getFullDate(createdAt, true),
    time: getFullHours(createdAt),
  };

  const bookmarkPoint = {
    ...defaultPoint,
    iconName: "heart",
    iconColor: "#E97272",
    title: translate("common.favorites"),
  };

  const ratePoint = {
    ...defaultPoint,
    iconName: "thumbsUp",
    iconColor: "#72B5E9",
    title: translate("common.evaluation"),
  };

  const adminUpdatePoint = {
    ...defaultPoint,
    iconName: "settings",
    iconColor: "#C5C9D2",
    title: translate("common.adminActions"),
  };

  const accessLogPoint = {
    ...defaultPoint,
    iconName: "document",
    iconColor: "#FD9F2B",
    title: translate("common.checkInOnThePlatform"),
  };

  const broadcastRoomPoint = {
    ...defaultPoint,
    iconName: "broadcastRooms",
    iconColor: "#E386DA",
    title: translate("entities.broadcastRooms.plural"),
  };

  const marketRoomPoint = {
    ...defaultPoint,
    iconName: "marketRooms",
    iconColor: "#E9A672",
    title: translate("entities.marketRooms.singular"),
  };

  const reactionPoint = {
    ...defaultPoint,
    iconName: "emojis",
    iconColor: "#77dd77",
    title: translate("common.reaction"),
  };

  const chatPoint = {
    ...defaultPoint,
    iconName: "chat",
    iconColor: "#72E9C5",
    title: translate("common.chat"),
  };

  const questionPoint = {
    ...defaultPoint,
    iconName: "question",
    iconColor: "#E9E172",
    title: translate("common.validQuestion"),
  };

  const connectionPoint = {
    ...defaultPoint,
    iconName: "addedUser",
    iconColor: "#A393F5",
    title: translate("common.connection"),
  };

  switch (type) {
    //* Questions
    case SENDED_QUESTION:
      return {
        ...questionPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.question.room")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Chat
    case SENDED_MESSAGE:
      return {
        ...chatPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.chat.firstMessage")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case SENDED_INDIVIDUAL_MESSAGE:
      return {
        ...chatPoint,
        description: translate(
          "entities.gamification.extractPoints.chat.firstIndividualMessage"
        ),
      };
    case REPLIED_MESSAGE:
      return {
        ...chatPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.chat.userReplied")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case REACTION_MESSAGE:
      return {
        ...chatPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.chat.userLiked")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Bookmarks
    case BOOKMARK_PARTNERS:
      return {
        ...bookmarkPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.bookmark.marketRoom"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case BOOKMARK_ROOMS:
      return {
        ...bookmarkPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.bookmark.broadcastRoom"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case BOOKMARK_SCHEDULES:
      return {
        ...bookmarkPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.bookmark.schedule")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case BOOKMARK_SPEAKERS:
      return {
        ...bookmarkPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.bookmark.speaker")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Rates
    case RATE_ROOM:
      return {
        ...ratePoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.rate.broadcastRoom"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case RATE_SCHEDULE:
      return {
        ...ratePoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.rate.schedule")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Admin update
    case ADMIN_UPDATE:
      return {
        ...adminUpdatePoint,
        description: (
          <>
            {points >= 0
              ? translate("entities.gamification.extractPoints.admin.addPoints")
              : translate(
                  "entities.gamification.extractPoints.admin.removePoints"
                )}{" "}
            <strong>
              {Math.abs(points)} {translate("common.points")}
            </strong>{" "}
            {points >= 0 ? "à" : "da"} sua pontuação
          </>
        ),
      };
    case ADMIN_RESET:
      return {
        ...adminUpdatePoint,
        description: translate(
          "entities.gamification.extractPoints.admin.resetPoints"
        ),
      };

    //*  Reactions
    case REACTION_BROADCAST_ROOM:
      return {
        ...reactionPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.reaction.broadcastRoom"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Access log
    case ACCESS_LOG_FIRST_ACCESS:
      return {
        ...accessLogPoint,
        description: translate(
          "entities.gamification.extractPoints.accessLog.firstAccess"
        ),
      };

    //* Broadcast rooms
    case ACCESS_LOG_BROADCAST:
      return {
        ...broadcastRoomPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.broadcastRoom.checkIn"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Connections
    case FRIEND_CONNECTION:
      return {
        ...connectionPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.connection.social")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };

    //* Market Rooms
    case ACCESS_LOG_MARKET_ROOM:
      return {
        ...marketRoomPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.marketRoom.checkIn"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case PARTNER_SOCIAL:
      return {
        ...marketRoomPoint,
        description: (
          <>
            {translate("entities.gamification.extractPoints.partner.social")}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    case PARTNER_EXTRA_BUTTON:
      return {
        ...marketRoomPoint,
        description: (
          <>
            {translate(
              "entities.gamification.extractPoints.partner.extraButton"
            )}{" "}
            <strong>{dTranslate(title)}</strong>
          </>
        ),
      };
    default:
      return {
        ...marketRoomPoint,
        description: "",
      };
  }
};

export const useExtractPoints = pointsData => {
  const { translate } = useTranslation();
  const { dTranslate } = useDynamicTranslation();

  const extractPoints = React.useMemo(
    () =>
      pointsData.data.map(extractPoint =>
        renderExtractPoint(extractPoint, translate, dTranslate)
      ),
    [dTranslate, pointsData.data, translate]
  );

  return { extractPoints };
};
