import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { Container, SpeakerInfos } from "./styles";
import { isDataValid } from "../../../../utils/validations";
import { Avatar } from "../../../atoms";

function SpeakerCell({ speaker, className, onClick }) {
  if (!speaker) return null;

  const { name, role, picture, company } = speaker && speaker;

  return (
    isDataValid(speaker) && (
      <Container id="speaker-cell" className={className} onClick={onClick}>
        <Avatar src={picture} />

        <SpeakerInfos>
          <h4 className="speaker-name">{name}</h4>
          <h4 className="speaker-role">
            {role} {company && `- ${company}`}
          </h4>
        </SpeakerInfos>
      </Container>
    )
  );
}

SpeakerCell.propTypes = {
  className: PropTypes.string,
  speaker: PropTypes.object.isRequired,
  onClick: PropTypes.func,
};

SpeakerCell.defaultProps = {
  className: null,
  onClick: () => {},
};

export default SpeakerCell;
