import styled, { keyframes } from "styled-components";

//* CUSTOM IMPORTS
import TextareaAutosize from "react-autosize-textarea";
import { darken } from "polished";
import { Modal } from "~/shared/components/molecules";

export const CustomModal = styled(Modal)`
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  position: relative;
  background: ${props => props.theme.colors.primary};
  border: none;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;

  svg {
    height: 20px;
    width: 20px;
    color: ${props => props.theme.header.text};
  }

  .question-area-title {
    width: 100%;
    text-align: center;
    margin: 0;
    color: ${props => props.theme.header.text};
    font-weight: bold;
    font-size: 16px;
  }

  .question-area-sent-message {
    margin-top: 16px;
    width: 100%;
    text-align: center;
    color: ${props => props.theme.header.text};
    opacity: 5;
    font-size: 14px;
  }
`;

export const QuestionInput = styled(TextareaAutosize)`
  margin-top: 16px;
  background: ${props =>
    props.isQuestionSent
      ? "transparent"
      : darken(0.05, props.theme.colors.primary)};
  border-radius: 10px;
  padding: 10px;
  border: none;
  min-height: 100px;
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: ${props => props.theme.header.text};

  &::placeholder {
    color: ${props => props.theme.header.text};
    opacity: 5;
  }
`;

// ANIMATION
export const showButton = keyframes`
  from{
    bottom: 0%;
  }

  to{
    bottom: calc(0% - 48px);
  }
`;

export const hideButton = keyframes`
  from{
    bottom: calc(0% - 48px);
  }

  to{
    bottom: 0%;
  }
`;

export const SendQuestionButton = styled.button`
  opacity: ${props => (props.active ? 1 : 0)};
  position: absolute;
  bottom: ${props => (props.active ? "calc(0% - 48px)" : "1%")};
  width: 100%;
  left: 0;
  border: none;
  box-shadow: 0px 2px 4px ${props => props.theme.colors.shadow};
  background: ${props => props.theme.colors.accent};
  border-radius: 0px 0px 8px 8px;
  padding: 24px 0 16px 0;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  z-index: -1;
  animation: ${props => (props.active ? showButton : hideButton)} 0.3s ease;
  transition: background 0.3s, opacity 1s;

  .sending {
    height: 20px;
    width: 20px;
  }

  &:hover {
    background: ${props => darken(0.15, props.theme.colors.accent)}};
  }
`;
