import React from "react";

//* APIs
import { showUserMeeting } from "../../apis/meetings";

//* CUSTOM IMPORTS
import { Typography, Box } from "~/shared/components/atoms";
import { SwipeTabs, SearchBar } from "~/shared/components/molecules";
import { ExpandableContainer } from "~/shared/components/molecules/ExpandableContainer";
import { useTranslation } from "~/shared/hooks/translation";
import { MeetingList } from "./components/MeetingList";
import { MeetingDetails } from "./components/MeetingDetails";
import { MyScheduleProvider, useMySchedule } from "./hooks";
import { MY_SCHEDULE_TYPES } from "~/shared/utils/types";
import { useWindow } from "~/shared/hooks/window";

const Content = () => {
  const { translate } = useTranslation();
  const { isBelowWide } = useWindow();

  //* STATES
  const { meetings } = useMySchedule();

  const [search, setSearch] = React.useState("");
  const [selectedSchedule, setSelectedSchedule] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    /** Get query params in url after fetch meetings API */
    if (meetings.isLoaded) {
      const urlParams = new URLSearchParams(window.location.search);
      const meetingId = urlParams.get("meetingId");

      /** If has an queryParams meetingId select the meeting in list with this ID. */
      if (meetingId) {
        const meetingType = MY_SCHEDULE_TYPES.MEETING;

        const findedMeeting = meetings.data.find(
          metting => metting.id === Number(meetingId)
        );

        /** If find an meeting in list with this id select it. */
        if (findedMeeting) {
          setSelectedSchedule({ ...findedMeeting, type: meetingType });
        } else {
          /** If do not find an meeting in list with the queryParams ID. Call an
           * show API then inset the response inside the list.
           */
          showUserMeeting({ meetingId }).then(response => {
            setSelectedSchedule({
              ...response.data,
              type: MY_SCHEDULE_TYPES.MEETING,
            });
          });
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [meetings.isLoaded]);

  const closeDetails = () => {
    setSelectedSchedule(null);
  };

  const handleOnScheduleItemClick = schedule => {
    setSelectedSchedule(null);

    if (selectedSchedule) {
      if (selectedSchedule.id === schedule.id) {
        closeDetails();
        return;
      }

      setSelectedSchedule(schedule);
    } else {
      setSelectedSchedule(schedule);
    }
  };

  const MY_SCHEDULE_TABS = [
    { id: 1, name: translate("entities.meetings.scheduledPlural") },
  ];

  const scheduleDetails = React.useMemo(() => {
    return (
      <Box
        style={{
          marginTop: isBelowWide ? 20 : 70,
          marginBottom: isBelowWide && 20,
        }}
      >
        {selectedSchedule &&
          selectedSchedule.type === MY_SCHEDULE_TYPES.MEETING && (
            <MeetingDetails data={selectedSchedule} onCancel={closeDetails} />
          )}
      </Box>
    );
  }, [isBelowWide, selectedSchedule]);

  return (
    <Box>
      <Typography className="centered bigTitle" style={{ marginBottom: 16 }}>
        {translate("entities.meetings.myMeetings")}
      </Typography>

      <SearchBar
        centerPlaceholder
        style={{ maxWidth: 450, margin: "0 auto" }}
        placeholder={translate("entities.meetings.searchPartner")}
        onChange={value => {
          setSearch(value);
          closeDetails();
        }}
      />

      <ExpandableContainer
        style={{ marginTop: 40 }}
        isExpanded={!isBelowWide && selectedSchedule}
        leftChildren={
          <SwipeTabs
            size="small"
            alignment="left"
            tabs={MY_SCHEDULE_TABS}
            style={{ maxWidth: 458, margin: "0 auto" }}
          >
            <MeetingList
              selectedMeetingId={selectedSchedule && selectedSchedule.id}
              search={search}
              onItemClick={handleOnScheduleItemClick}
              detailsComponent={isBelowWide && scheduleDetails}
            />
          </SwipeTabs>
        }
        rightChildren={scheduleDetails}
      />
    </Box>
  );
};

const MySchedule = () => (
  <MyScheduleProvider>
    <Content />
  </MyScheduleProvider>
);

export default MySchedule;
