import api from "~/shared/services/api";

export const getRanking = ({ page, search }) =>
  api.get("/ranking", {
    params: {
      search,
      page,
    },
  });

export const listUserPointsHistory = ({ page, userId }) =>
  api.get(`user_point/history/${userId}`, { params: { page } });

export const showUserPoints = ({ userId }) => api.get(`user_point/${userId}`);

export const getGamificationSettings = () => api.get("/gamification_config");
