import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  list: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
    bannerId: null,
  },
};

export default function adminGroupsReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST
      case types.LIST.SUCCESSFUL: {
        const { banners } = action.payload;

        if (isDataValid(banners)) {
          const formattedFields = banners.map(field => {
            return { ...field, id: String(field.id) };
          });

          draft.list.data = formattedFields;
        }

        draft.list.isLoaded = true;

        break;
      }
      case types.LIST.SET: {
        const { banners } = action.payload;

        draft.list.data = banners;

        break;
      }
      case types.LIST.CLEAR: {
        draft.list.data = [];
        draft.list.isLoaded = false;

        break;
      }

      // CREATE
      case types.CREATE.REQUEST: {
        draft.list.isCreating = "PENDING";
        break;
      }
      case types.CREATE.SUCCESSFUL: {
        const { banner } = action.payload;

        if (isDataValid(banner)) {
          const formattedData = {
            ...banner,
            id: String(banner.id),
          };

          draft.list.data = [formattedData, ...draft.list.data];
        }

        draft.list.isCreating = "FINISH";
        break;
      }
      case types.CREATE.FAILED: {
        draft.list.isCreating = "FAILED";
        break;
      }

      // UPDATE
      case types.UPDATE.REQUEST: {
        const { bannerId } = action.payload;

        draft.list.isUpdating = "PENDING";
        draft.list.bannerId = bannerId;
        break;
      }
      case types.UPDATE.SUCCESSFUL: {
        const { banner } = action.payload;

        if (isDataValid(banner)) {
          const auxArray = draft.list.data;
          const index = auxArray.findIndex(obj => obj.id === String(banner.id));

          auxArray.splice(index, 1, {
            ...banner,
            id: String(banner.id),
          });

          draft.list.data = auxArray;
        }

        draft.list.isUpdating = "FINISH";
        draft.list.bannerId = null;
        break;
      }
      case types.UPDATE.FAILED: {
        draft.list.isUpdating = "FAILED";
        draft.list.bannerId = null;
        break;
      }

      // DELETE
      case types.DELETE.REQUEST: {
        draft.list.isDeleting = "PENDING";
        break;
      }
      case types.DELETE.SUCCESSFUL: {
        const { bannerId } = action.payload;

        const filteredArray = draft.list.data.filter(
          obj => obj.id !== String(bannerId)
        );

        draft.list.data = filteredArray;
        draft.list.isDeleting = "FINISH";

        break;
      }
      case types.DELETE.FAILED: {
        draft.list.isDeleting = "FAILED";
        break;
      }

      // FORM
      case types.EVENTS.CLEAR: {
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.bannerId = null;

        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.list.data = [];
        draft.list.isCreating = null;
        draft.list.isDeleting = null;
        draft.list.isUpdating = null;
        draft.list.isLoaded = false;
        draft.list.bannerId = null;

        break;
      }
      default:
    }
  });
}
