import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIS
import {
  listUserPointsHistory,
  showUserPoints,
} from "~/modules/signed/modules/client/apis/gamification";
import { API_STATUS } from "~/shared/utils/apis";

//* CUSTOM IMPORTS
const ProfileGamificationContext = createContext({});

const ProfileGamificationProvider = ({ children }) => {
  //* STATES
  const [pointsExtract, setPointsExtract] = React.useState({
    data: [],
    page: 1,
    lastPage: 1,
  });
  const [pointsExtractApiStatus, setPointsExtractApiStatus] = React.useState(
    API_STATUS.IDLE
  );
  const [profilePoints, setProfilePoints] = React.useState(null);

  //* FUNCTIONS
  const getProfilePoints = React.useCallback(userId => {
    showUserPoints({ userId }).then(response =>
      setProfilePoints(response.data)
    );
  }, []);

  const getUserPointsExtract = React.useCallback((page, userId) => {
    listUserPointsHistory({ userId, page }).then(response => {
      setPointsExtract({ ...response.data, isLoaded: true });
    });
  }, []);

  const paginateUserPointsExtract = React.useCallback((page, userId) => {
    setPointsExtractApiStatus(API_STATUS.PAGING);

    listUserPointsHistory({ userId, page })
      .then(response => {
        setPointsExtract(state => ({
          ...state,
          ...response.data,
          data: [...state.data, ...response.data.data],
        }));

        setPointsExtractApiStatus(API_STATUS.SUCCESS);
      })
      .catch(() => {
        setPointsExtractApiStatus(API_STATUS.FAILED);
      });
  }, []);

  return (
    <ProfileGamificationContext.Provider
      value={{
        //* States
        profilePoints,
        pointsExtract,
        pointsExtractApiStatus,

        //* Function
        getProfilePoints,
        getUserPointsExtract,
        paginateUserPointsExtract,
      }}
    >
      {children}
    </ProfileGamificationContext.Provider>
  );
};

ProfileGamificationProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useProfileGamification = () => {
  const context = useContext(ProfileGamificationContext);

  if (!context)
    throw new Error(
      "useProfileGamification must be used within ProfileGamificationProvider"
    );

  return context;
};

export { ProfileGamificationProvider, useProfileGamification };
