import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Container, LeftContainer, RightContainer, Line } from "./styles";

const expandableContentPropTypes = {
  /** If <b>true</b> the <b>RightContainer</b> will be expanded with
   * an animation.
   */
  isExpanded: PropTypes.bool,
  /** The element to be rendered at left of this component. */
  leftChildren: PropTypes.element.isRequired,
  /** The element to be rendered at right of this component. */
  rightChildren: PropTypes.element,
};

const expandableContentDefaultProps = {
  isExpanded: false,
  rightChildren: null,
};

/** This component is very flexible an is commonly used with an <b>CellList</b>
 * at left and some Information of an Item at right.
 */
const ExpandableContainer = ({
  isExpanded,
  leftChildren,
  rightChildren,
  ...rest
}) => {
  return (
    <Container {...rest}>
      <LeftContainer>{leftChildren}</LeftContainer>

      <Line size={3} orientation="vertical" isExpanded={isExpanded} />

      {rightChildren && (
        <RightContainer isExpanded={isExpanded}>{rightChildren}</RightContainer>
      )}
    </Container>
  );
};

ExpandableContainer.propTypes = expandableContentPropTypes;

ExpandableContainer.defaultProps = expandableContentDefaultProps;

export {
  ExpandableContainer,
  expandableContentPropTypes,
  expandableContentDefaultProps,
};
