import React from "react";
import PropTypes from "prop-types";
import "react-quill/dist/quill.snow.css";
import { ThemeContext } from "styled-components";

// CUSTOM IMPORTS
import { languagesMenuPropTypes } from "../../molecules";
import { isDataValid } from "../../../utils/validations";
import { dropdownPropTypes } from "../Dropdown";
import { Container, ReactQuill, Dropdown, LanguagesMenu } from "./styles";
import { useTranslation } from "../../../hooks/translation";

const richTextEditorPropTypes = {
  value: PropTypes.string.isRequired,

  /** If <b>true</b>, disable the user to write in this component */
  readOnly: PropTypes.bool,

  /** Callback that is triggered every time the value of this component change. Return
   * the new value.
   *
   * Example: (value) => {}
   *
   */
  onChange: PropTypes.func,

  /** An array of options, if this props has data render an <b>Dropdown</b> at
   * rigth of the component.
   */
  variables: dropdownPropTypes.options,

  /** If this props has data, render an <b>LanguageMenu</b> in the component.
   * See all prop sin <b>Molecules/LanguageMenu</b>
   */
  languageMenu: PropTypes.shape(languagesMenuPropTypes),
};

const richTextEditorDefaultProps = {
  variables: [],
  readOnly: false,
  onChange: () => {},
  languageMenu: null,
};

/**
 * RichTextEditor is an HTML text editor.
 * This component needs to be inside an <b>Form</b>. */
const RichTextEditor = React.memo(
  ({ value, readOnly, onChange, variables, languageMenu, ...rest }) => {
    const { colors } = React.useContext(ThemeContext);
    const { translate } = useTranslation();

    const modules = {
      toolbar: {
        container: [
          [
            { font: [] },
            "bold",
            "italic",
            "underline",
            { size: ["small", false, "large", "huge"] },
            {
              color: [
                colors.primary,
                colors.primaryDark,
                colors.accent,
                "#000000",
                "#e60000",
                "#ff9900",
                "#ffff00",
                "#008a00",
                "#0066cc",
                "#9933ff",
                "#ffffff",
                "#facccc",
                "#ffebcc",
                "#ffffcc",
                "#cce8cc",
                "#cce0f5",
                "#ebd6ff",
                "#bbbbbb",
                "#f06666",
                "#ffc266",
                "#ffff66",
                "#66b966",
                "#66a3e0",
                "#c285ff",
                "#888888",
                "#a10000",
                "#b26b00",
                "#b2b200",
                "#006100",
                "#0047b2",
                "#6b24b2",
                "#444444",
                "#5c0000",
                "#663d00",
                "#666600",
                "#003700",
                "#002966",
                "#3d1466",
              ],
            },
            { list: "ordered" },
            { list: "bullet" },
            { align: [] },
            "link",
            "clean",
          ],
        ],
      },
      clipboard: { matchVisual: false },
    };

    const emptyModules = {
      toolbar: {
        container: [],
      },
      clipboard: { matchVisual: false },
    };

    const [cursorPosition, setCursorPosiiton] = React.useState(0);

    const reactQuillRef = React.useRef(null);

    React.useEffect(() => {
      // Disable spell check
      if (isDataValid(reactQuillRef.current))
        reactQuillRef.current.editor.root.setAttribute("spellcheck", "false");
    }, []);

    const getCursorPosition = () => {
      if (reactQuillRef.current) {
        const editor = reactQuillRef.current.getEditor();
        const unprivilegedEditor = reactQuillRef.current.makeUnprivilegedEditor(
          editor
        );

        const range = unprivilegedEditor.getSelection();

        const position = range ? range.index : 0;

        return position;
      }

      return 0;
    };

    const addVariable = selectedVariable => {
      if (reactQuillRef.current) {
        const editor = reactQuillRef.current.getEditor();

        editor.insertText(cursorPosition, `{{${selectedVariable}}}`);
      }
    };

    const onDropdownClick = () => {
      setCursorPosiiton(getCursorPosition());
    };

    return (
      <Container {...rest}>
        {languageMenu && <LanguagesMenu {...languageMenu} />}

        {!readOnly && variables.length > 0 && (
          <Dropdown
            fixedPlaceholder
            placeholder={translate("common.variable")}
            name="variables"
            options={variables}
            onClick={onDropdownClick}
            onChange={addVariable}
          />
        )}

        <ReactQuill
          colors={colors}
          readOnly={readOnly}
          ref={reactQuillRef}
          theme="snow"
          value={value}
          onChange={newValue => onChange(newValue)}
          modules={!readOnly ? modules : emptyModules}
        />
      </Container>
    );
  }
);

RichTextEditor.propTypes = richTextEditorPropTypes;

RichTextEditor.defaultProps = richTextEditorDefaultProps;

export { RichTextEditor, richTextEditorPropTypes, richTextEditorDefaultProps };
