import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import CellLoader from "~/shared/components/molecules/cellLoaders/FieldsetLoader";
import {
  ContentLoader,
  Container,
  Fieldset,
  AttributesContainer,
  SettingsContainer,
} from "./styles";
import { Trans, Typography, Icon } from "~/shared/components/atoms";
import { SimpleMenu } from "~/shared/components/molecules";
import { INPUT_TYPES } from "~/shared/utils/inputs";
import { useProfile } from "../../../../../hooks";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";
import { useTranslation } from "~/shared/hooks/translation";
import { UserTags } from "./components/Tags";

function UserAttributeList({ user, hasSettings, callIf }) {
  const { id } = user;

  const {
    listProfileSettings,
    changeProfileSettings,
    profileSettings,
    settingsIsLoaded,
  } = useProfile();
  const { dTranslate } = useDynamicTranslation();
  const { translate } = useTranslation();

  React.useEffect(() => {
    if (callIf) listProfileSettings(id);
  }, [id, callIf, listProfileSettings]);

  // FUNCTIONS
  const getAttributeValue = refCode => {
    const attribute = Object.entries(user.attributes).find(
      entry => entry[0] === refCode
    );

    return attribute ? attribute[1] : null;
  };

  function setAttributeValue(refCode, isJson) {
    let attribute = null;

    if (user.attributes)
      Object.entries(user.attributes).forEach(([key, value]) => {
        if (refCode === key) {
          if (isJson) {
            attribute = value && value.value;
          } else {
            attribute = value;
          }
        }
      });

    return attribute === null ? (
      <small>
        <Trans translationKey="common.unavailable">Indisponível</Trans>
      </small>
    ) : (
      attribute
    );
  }

  const handleChangeVisibilityToAllow = value => {
    const { cadaster_item_id: cadasterItemId } = value;

    const data = [
      {
        cadaster_item_id: cadasterItemId,
        is_visible: true,
      },
    ];

    changeProfileSettings(id, data);
  };

  const handleChangeVisibilityToNotAllow = value => {
    const { cadaster_item_id: cadasterItemId } = value;

    const data = [
      {
        cadaster_item_id: cadasterItemId,
        is_visible: false,
      },
    ];

    changeProfileSettings(id, data);
  };

  const fillItems = value => {
    const items = [
      {
        id: 1,
        name: (
          <Typography>
            {translate("entities.profile.body.general.showInProfile")}
          </Typography>
        ),
        function: () => handleChangeVisibilityToAllow(value),
      },
      {
        id: 2,
        name: (
          <Typography>
            {translate("entities.profile.body.general.dontShowInProfile")}
          </Typography>
        ),
        function: () => handleChangeVisibilityToNotAllow(value),
      },
    ];

    return items;
  };

  function renderField(field) {
    const { cadasterItems, is_visible: isVisible } = field;

    if (
      cadasterItems.ref_code !== "name" &&
      cadasterItems.ref_code !== "cpf" &&
      cadasterItems.ref_code !== "password"
    ) {
      if (cadasterItems.type === "tags") {
        return (
          <>
            {getAttributeValue(cadasterItems.ref_code) ? (
              <Fieldset key={cadasterItems.ref_code}>
                <AttributesContainer>
                  <Typography
                    className="subTitle bold"
                    style={{ marginBottom: "8px" }}
                  >
                    {dTranslate(cadasterItems.title)}
                  </Typography>

                  <UserTags
                    selectedTags={getAttributeValue(cadasterItems.ref_code)}
                    refCode={cadasterItems.ref_code}
                  />
                </AttributesContainer>
                {hasSettings && (
                  <SettingsContainer>
                    <Typography
                      className="regular smoked"
                      style={{ marginRight: "8px" }}
                    >
                      {isVisible
                        ? translate(
                            "entities.profile.body.general.showInProfile"
                          )
                        : translate(
                            "entities.profile.body.general.dontShowInProfile"
                          )}
                    </Typography>
                    <SimpleMenu
                      fillItems={() => fillItems(field)}
                      buttonIcon={<Icon iconName="menu" />}
                    />
                  </SettingsContainer>
                )}
              </Fieldset>
            ) : (
              <>
                <Typography
                  className="subTitle bold"
                  style={{ marginBottom: "8px" }}
                >
                  {dTranslate(cadasterItems.title)}
                </Typography>
                <small style={{ opacity: "50%" }}>
                  <Trans translationKey="common.unavailable">
                    Indisponível
                  </Trans>
                </small>
              </>
            )}
          </>
        );
      }
      return (
        <Fieldset key={cadasterItems.ref_code}>
          <AttributesContainer>
            <Typography
              className="subTitle bold"
              style={{ marginBottom: "8px" }}
            >
              {dTranslate(cadasterItems.title)}
            </Typography>

            <Typography className={isVisible ? "regular" : "regular opaque"}>
              {setAttributeValue(
                cadasterItems.ref_code,
                cadasterItems.type === INPUT_TYPES.PHONE.value
              )}
            </Typography>
          </AttributesContainer>
          {hasSettings && (
            <SettingsContainer>
              <Typography
                className="regular smoked"
                style={{ marginRight: "8px" }}
              >
                {isVisible
                  ? translate("entities.profile.body.general.showInProfile")
                  : translate(
                      "entities.profile.body.general.dontShowInProfile"
                    )}
              </Typography>
              <SimpleMenu
                fillItems={() => fillItems(field)}
                buttonIcon={<Icon iconName="menu" />}
              />
            </SettingsContainer>
          )}
        </Fieldset>
      );
    }

    return <></>;
  }

  return settingsIsLoaded ? (
    <Container>{profileSettings.map(field => renderField(field))}</Container>
  ) : (
    <ContentLoader cellLoader={<CellLoader />} size={5} />
  );
}

UserAttributeList.propTypes = {
  user: PropTypes.object.isRequired,
  hasSettings: PropTypes.bool,
  callIf: PropTypes.bool.isRequired,
};

UserAttributeList.defaultProps = {
  hasSettings: false,
};

export default UserAttributeList;
