import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography } from "../Typography";
import { Container, RadioButton } from "./styles";

const radioPropTypes = {
  /** Define the name of <b>Radio</b>, if there is another <b>Radio</b> into DOM
   * with the same name, when you select one <b>Radio</b> the other ones will be unselected
   */
  name: PropTypes.string.isRequired,

  /** Optional, render an label to the right of <b>Radio</b>  */
  label: PropTypes.string,

  style: PropTypes.object,
};

const radioDefaultProps = {
  label: null,
  style: null,
};

/** Radio is commonly used when you want to force user to choose an option
 * when tou have multiple Radios the user need to choose one of then
 */
const Radio = ({ label, name, style, ...rest }) => {
  return (
    <Container style={style}>
      <RadioButton type="radio" name={name} {...rest} />

      {label && (
        <Typography className="subTitle" style={{ marginLeft: 10 }}>
          {label}
        </Typography>
      )}
    </Container>
  );
};

Radio.propTypes = radioPropTypes;

Radio.defaultProps = radioDefaultProps;

export { Radio, radioPropTypes, radioDefaultProps };
