import styled, { css } from "styled-components";
import { lighten } from "polished";

export const Container = styled.div`
  transition: all 0.3s;
  border: none;
  background: none;
  outline: none;
  cursor: ${props => !props.disableHoverEffect && "pointer"};
  border-radius: ${props => props.borderRadius}px;

  background-color: ${props => {
    const { variant, theme, hovered } = props;

    if (hovered)
      switch (variant) {
        case "accentBackground":
          return lighten(0.35, theme.colors.accent);

        case "background":
          return lighten(0.3, theme.colors.hover);

        default:
          break;
      }
  }};

  ${props => {
    const { variant, disableHoverEffect, theme } = props;

    if (!disableHoverEffect)
      return css`
        &:hover {
          ${() => {
            switch (variant) {
              case "background":
                return css`
                  background-color: ${theme.colors.hover};
                `;

              case "accentBackground":
                return css`
                  background-color: ${lighten(0.2, theme.colors.accent)};
                `;

              default:
                return css`
                  transform: scale(1.015);
                `;
            }
          }}
        }
      `;
  }}
`;
