import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  Container,
  HeaderFragment,
  Wallpaper,
  Logo,
  Tags,
  SocialMidias,
  ContactButtons,
  BookmarkButton,
  ScheduleButton,
} from "./styles";
import { Icon, Line } from "~/shared/components/atoms";
import { validateImage } from "~/shared/utils/images";
import { useTranslation } from "~/shared/hooks/translation";
import { getFullHours, getFullDateBR } from "~/shared/utils/dates";
import { createActiveLog } from "../../../../apis/activeLog";
import { ACTIVE_LOG_TYPES } from "~/shared/utils/types";

function MarketRoomHeader({
  partner,
  onSchedule,
  disableScheduleButton,
  scheduledMeeting,
}) {
  const {
    id,
    title,
    sub_title: subTitle,
    logo,
    wallpaper,
    tags,
    facebook_url: facebookUrl,
    instagram_url: instagramUrl,
    twitter_url: twitterUrl,
    linkedin_url: linkedinUrl,
    website_url: websiteUrl,
    extraButtons,
    bookmark,
  } = partner && partner;

  const { translate } = useTranslation();

  const hasScheduledMeeting = !!scheduledMeeting;

  //* FUNCTIONS
  const handleOnSocialMidiaClick = socialMidiaName => {
    switch (socialMidiaName) {
      case "facebook":
        createActiveLog({
          action: ACTIVE_LOG_TYPES.PARTNER_SOCIAL_FACEBOOK,
          refId: partner.id,
        });
        break;
      case "twitter":
        createActiveLog({
          action: ACTIVE_LOG_TYPES.PARTNER_SOCIAL_TWITTER,
          refId: partner.id,
        });
        break;
      case "linkedin":
        createActiveLog({
          action: ACTIVE_LOG_TYPES.PARTNER_SOCIAL_LINKEDIN,
          refId: partner.id,
        });
        break;
      case "instagram":
        createActiveLog({
          action: ACTIVE_LOG_TYPES.PARTNER_SOCIAL_INSTAGRAM,
          refId: partner.id,
        });
        break;
      case "website": {
        createActiveLog({
          action: ACTIVE_LOG_TYPES.PARTNER_SOCIAL_SITE,
          refId: partner.id,
        });
        break;
      }

      default:
        break;
    }
  };

  const handleOnExtraButtonClick = () => {
    createActiveLog({
      action: ACTIVE_LOG_TYPES.PARTNER_EXTRA_BUTTON,
      refId: partner.id,
    });
  };

  return (
    <>
      <Container id="market-room-header">
        <Wallpaper src={validateImage(wallpaper)} />

        <footer id="market-room-footer-header">
          <HeaderFragment>
            <Logo src={logo} size={160} />

            <div className="header-fragment-div">
              <h4 className="market-room-title">{title}</h4>
              <small className="market-room-subTitle">{subTitle}</small>

              <Tags tags={tags} wrap />
              <SocialMidias
                className="social-midias"
                facebookUrl={facebookUrl}
                instagramUrl={instagramUrl}
                twitterUrl={twitterUrl}
                linkedinUrl={linkedinUrl}
                websiteUrl={websiteUrl}
                onClick={socialMidiaName =>
                  handleOnSocialMidiaClick(socialMidiaName)
                }
              />
            </div>
          </HeaderFragment>

          <ContactButtons
            id="market-room-header-buttons"
            extraButtons={extraButtons}
            onClick={handleOnExtraButtonClick}
          />

          {!disableScheduleButton && (
            <ScheduleButton
              tipSize={150}
              tip={
                hasScheduledMeeting && translate("entities.meetings.visualize")
              }
              variant={hasScheduledMeeting ? "outlined" : "contained"}
              icon={<Icon iconName="schedule" />}
              onClick={onSchedule}
            >
              {hasScheduledMeeting
                ? `${getFullDateBR(scheduledMeeting.start_date)} ${translate(
                    "common.at"
                  )}  ${getFullHours(scheduledMeeting.start_date)}`
                : translate("common.scheduleMeeting")}
            </ScheduleButton>
          )}

          <BookmarkButton isSelected={bookmark} apiType="partners" apiId={id} />
        </footer>
      </Container>
      <Line />
    </>
  );
}

MarketRoomHeader.propTypes = {
  partner: PropTypes.object.isRequired,
  onSchedule: PropTypes.func.isRequired,
  /** If true, will hide the ScheduleButton */
  disableScheduleButton: PropTypes.bool.isRequired,
  /** This state indicates if user has an scheduled meeting with partner */
  scheduledMeeting: PropTypes.object.isRequired,
};

export default MarketRoomHeader;
