import React, {
  createContext,
  useContext,
  useState,
  useCallback,
  useEffect,
} from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

//* ACTIONS
import {
  listAdminRegistrationFields,
  clearAdminRegistrationFields,
} from "../../../../../store/registrationFields/actions";

//* APIs
import {
  importUserSheet,
  validateUserImport,
  validateUserImportSingleData,
} from "../../../../../apis/users";
import { uploadFile } from "../../../../../../../apis/files";

//* CUSTOM IMPORTS
import { ImportSocketProvider } from "../../../../../hooks/webSocket/channels/import";
import { toSnakeCase } from "~/shared/utils/strings";
import { getFullDate } from "~/shared/utils/dates";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

const ImportUsersContext = createContext(null);

const VALIDATION_STATUS = {
  NOT_VALIDATED: 1,
  VALIDATED_WITH_ERRORS: 2,
  VALIDATED: 3,
};

const TABS_INDEX = {
  IMPORT: 0,
  MATCH: 1,
  SETTINGS: 2,
  REVIEW: 3,
};

const SHEET_CONFIG_INITIAL = {
  passwordField: null,
  sendEmail: true,
  groups: [],
};

const ImportUsersProvider = ({ children }) => {
  const { NOT_VALIDATED, VALIDATED, VALIDATED_WITH_ERRORS } = VALIDATION_STATUS;

  const dispatch = useDispatch();
  const { translate } = useTranslation();
  const { data: refucerCadasterItems } = useSelector(
    state => state.adminRegistrationFields.list
  );

  //* STATES
  const [sheetData, setSheetData] = useState([]);
  const [tabIndex, setTabIndex] = useState(0);
  const [sheetDataHasChanges, setSheetDataHasChanged] = useState(false);
  const [sheetColumns, setSheetColumns] = useState([]);
  const [importErrors, setImportErrors] = useState([]);
  const [sheetFile, setSheetFile] = useState(null);
  const [isValidating, setIsValidating] = useState(false);
  const [rowLoading, setRowLoading] = useState(null);
  const [isImporting, setIsImporting] = useState(false);
  const [validationFailed, setFalidationVailed] = useState(false);
  const [sheetConfig, setSheetConfig] = useState(SHEET_CONFIG_INITIAL);
  const [requiredCadasterItems, setRequiredCadasterItems] = useState([]);
  const [validationStatus, setValidationStatus] = useState(NOT_VALIDATED);
  const [isImportErrorsChanged, setIsImportErrorsChanged] = useState(false);
  const [importLog, setImportLog] = useState([]);
  const [importLogPagination, setImportLogPagination] = React.useState({
    page: 1,
    lastPage: 2,
  });

  //* FUNCTIONS
  const { dTranslate } = useDynamicTranslation();
  const { setBreadCrumbs } = useCMS();

  const onBeforeUnload = useCallback(
    e => {
      if (sheetData.length > 0) e.returnValue = "Quer sair";
    },
    [sheetData.length]
  );

  const getTranslatedCadasterItems = React.useMemo(() => {
    if (refucerCadasterItems.length)
      return refucerCadasterItems.map(item => ({
        ...item,
        title: dTranslate(item.title),
      }));

    return refucerCadasterItems;
  }, [refucerCadasterItems, dTranslate]);

  const cadasterItems = getTranslatedCadasterItems;

  useEffect(() => {
    window.addEventListener("beforeunload", onBeforeUnload);

    return () => {
      window.removeEventListener("beforeunload", onBeforeUnload);
    };
  }, [onBeforeUnload]);

  useEffect(() => {
    dispatch(listAdminRegistrationFields());

    return () => {
      dispatch(clearAdminRegistrationFields());
    };
  }, [dispatch, onBeforeUnload]);

  useEffect(() => {
    return () => {
      setBreadCrumbs([]);
    };
  }, [setBreadCrumbs]);

  const getUnrelatedRequiredItems = useCallback(
    columns => {
      const requiredItems = getTranslatedCadasterItems.filter(
        item => item.required
      );
      const relatedColumns = columns.filter(
        column => column.correspondingField
      );

      /** If has no related column with cadaster items return all required cadaster items */
      if (relatedColumns.length === 0) {
        setRequiredCadasterItems(requiredItems);
      }

      /** Return all unrelated cadaster items */
      setRequiredCadasterItems(
        requiredItems.filter(
          item =>
            !relatedColumns.some(
              column => column.correspondingField.id === item.id
            )
        )
      );
    },
    [getTranslatedCadasterItems]
  );

  const formatSheetData = value => {
    if (value instanceof Date) return getFullDate(value);

    return String(value);
  };

  const clearData = React.useCallback(() => {
    setSheetColumns([]);
    setSheetData([]);
    setImportErrors([]);
    setSheetConfig(SHEET_CONFIG_INITIAL);
    setValidationStatus(NOT_VALIDATED);
    setSheetFile(null);
    setIsImportErrorsChanged(false);
  }, [NOT_VALIDATED]);

  const receiveSheetData = useCallback(
    (data, columns) => {
      const auxColumns = [];
      let auxSheetData = [];
      const noCorrespondingIndexes = [];

      columns.forEach((c, index) => {
        const correspondingField = getTranslatedCadasterItems.find(
          item => toSnakeCase(item.title) === c
        );

        if (!correspondingField) noCorrespondingIndexes.push(index);

        auxColumns.push({
          id: index,
          name: c,
          correspondingField: correspondingField || null,
        });
      });

      getUnrelatedRequiredItems(auxColumns);

      auxSheetData = data.map(d => {
        const cell = {};

        /** Add all attributes that do not have any corresponding field */
        Object.entries(d).forEach((entry, entryIndex) => {
          const key = entry[0];
          const value = entry[1];

          if (noCorrespondingIndexes.includes(entryIndex))
            cell[key] = formatSheetData(value);
        });

        /** Add all attributes that has an corresponding field, and all
        /**cadaster items that do not correspond an field with "null" */
        getTranslatedCadasterItems.forEach(item => {
          const correspondingKey = Object.keys(d).find(
            key => key === toSnakeCase(item.title)
          );

          if (correspondingKey) {
            cell[item.ref_code] = formatSheetData(d[correspondingKey]);
          } else {
            cell[item.ref_code] = null;
          }
        });

        return cell;
      });

      setSheetColumns(auxColumns);
      setSheetData(auxSheetData);
    },
    [getTranslatedCadasterItems, getUnrelatedRequiredItems]
  );

  /** Try to find an colunm that is related with an cadaster field by ref_code  */
  const getColumnByRefCode = useCallback(
    fieldRefCode => {
      /** Find the column that is related with the field  */
      return sheetColumns.find(column => {
        const { correspondingField } = column;

        if (!correspondingField) return false;

        if (correspondingField.ref_code === fieldRefCode) return true;

        return false;
      });
    },
    [sheetColumns]
  );

  /** Try to find an colunm that is related with an cadaster field by ref_code  */
  const getCadasterItemByRefCode = useCallback(
    refCode => {
      /** Find the column that is related with the field  */
      return getTranslatedCadasterItems.find(cadasterItem => {
        if (cadasterItem.ref_code === refCode) return true;

        return false;
      });
    },
    [getTranslatedCadasterItems]
  );

  const hasRelatedColumn = (field, column) => {
    if (!field) return null;

    const relatedColumn = getColumnByRefCode(field.ref_code);

    /** Check if the relatedColumn is not the same column as the current */
    if (relatedColumn) if (relatedColumn.name !== column.name) return true;

    return false;
  };

  /** Function to relate an column with an cadaster item */
  const relateFieldToColumn = useCallback(
    (field, column) => {
      const { correspondingField } = column;
      const { ref_code: refCode } = field;

      const auxSheetColumns = sheetColumns;
      let auxSheetData = [];

      const columnIndex = auxSheetColumns.findIndex(c => c.id === column.id);

      auxSheetColumns[columnIndex] = {
        ...auxSheetColumns[columnIndex],
        name: field.title,
        correspondingField: field,
      };

      auxSheetData = sheetData.map(cell => {
        const formmatedCell = cell;

        /** If column is already related to an field just update to the value
        /** with the new column */
        if (correspondingField) {
          formmatedCell[refCode] = cell[correspondingField.ref_code];
        } else {
          formmatedCell[refCode] = cell[column.name];

          /** Delete the unrelated property */
          delete formmatedCell[column.name];
        }

        return formmatedCell;
      });

      getUnrelatedRequiredItems(auxSheetColumns);
      setSheetColumns(auxSheetColumns);
      setSheetData(auxSheetData);
    },
    [getUnrelatedRequiredItems, sheetColumns, sheetData]
  );

  const getSheetCellDataByCadasterItem = useCallback(
    (refCode, index) => {
      const data = Object.values(sheetData[index]).find(
        (value, valueIndex) =>
          Object.keys(sheetData[index])[valueIndex] === refCode
      );

      return data || null;
    },
    [sheetData]
  );

  /** This function will update a specific value inside the sheet */
  const updateSheetCellData = useCallback(
    (value, lineIndex, cellKey) => {
      const auxSheetData = sheetData;

      auxSheetData[lineIndex][cellKey] = value;

      setSheetData(auxSheetData);
      setSheetDataHasChanged(true);
    },
    [sheetData]
  );

  /** This function will remove to the importErrors list, all the corrected
  /**errors */
  const clearCorrectedErrors = useCallback(() => {
    setImportErrors(
      importErrors.filter(error => {
        const { errors } = error;

        return !!errors;
      })
    );

    setIsImportErrorsChanged(true);
    setSheetDataHasChanged(false);
  }, [importErrors]);

  /** This function will show all rows inside the sheet that don not have errors */
  const dataToBeImported = useCallback(
    () => sheetData.length - importErrors.length,
    [importErrors.length, sheetData.length]
  );

  const setGroups = groups => setSheetConfig({ ...sheetConfig, groups });

  const setSendEmail = sendEmail =>
    setSheetConfig({ ...sheetConfig, sendEmail });

  const setPasswordField = passwordField =>
    setSheetConfig({ ...sheetConfig, passwordField });

  //* APIs CALLS
  /** Call an API to check if the import will be succeeded */
  const validateSheetImport = useCallback(() => {
    const validate = async () => {
      setIsValidating(true);
      setFalidationVailed(false);

      await validateUserImport({
        importData: sheetData,
        config: null,
      })
        .then(response => {
          const { errors } = response.data;

          setValidationStatus(
            errors.length > 0 ? VALIDATED_WITH_ERRORS : VALIDATED
          );
          setImportErrors(errors);
          setIsValidating(false);
        })
        .catch(() => {
          setIsValidating(false);
          setFalidationVailed(true);
        });
    };

    validate();
  }, [VALIDATED, VALIDATED_WITH_ERRORS, sheetData]);

  const validateSheetImportSingleData = useCallback(
    (importData, index) => {
      const validate = async () => {
        setRowLoading(index);

        await validateUserImportSingleData({ importData }).then(response => {
          const { errors } = response.data;

          const auxImportErrors = importErrors;

          auxImportErrors[index] = {
            ...auxImportErrors[index],
            errors: errors[0],
          };

          setImportErrors(auxImportErrors);
        });

        setRowLoading(null);
      };

      validate();
    },
    [importErrors]
  );

  /** This function import all the data stored to Server */
  const importSheet = useCallback(
    onSucess => {
      if (!sheetConfig || !sheetFile) return;

      const { groups, passwordField, sendEmail } = sheetConfig;

      const callImport = async () => {
        setIsImporting("PENDING");

        const file = new FormData();

        file.append("file", sheetFile);

        await uploadFile({ file }).then(response => {
          const { url } = response.data;

          const validData = sheetData.filter(
            (data, index) => !importErrors.some(error => error.index === index)
          );

          importUserSheet({
            importData: validData,
            config: {
              password_field: passwordField ? passwordField.ref_code : null,
              send_email: sendEmail,
              group_ids: groups.map(g => g.id),
              file: url,
              file_name: sheetFile.name,
            },
          })
            .then(importResponse => {
              const newImport = importResponse.data;

              if (newImport) {
                onSucess(newImport);
                setIsImporting("SUCCESS");

                /** If is the fist page add the new import into import log */
                if (importLogPagination.page === 1)
                  setImportLog([newImport, ...importLog]);
              }
            })
            .catch(() => setIsImporting("FAILED"));
        });
      };

      callImport();
    },
    [
      importErrors,
      importLog,
      importLogPagination.page,
      sheetConfig,
      sheetData,
      sheetFile,
    ]
  );

  const getCadasterItemName = useCallback(
    code => {
      const cadasterItem = getCadasterItemByRefCode(code);

      return cadasterItem && cadasterItem.title;
    },
    [getCadasterItemByRefCode]
  );

  /** Convert the errorsImport to an matrix to be added into an Table */
  const errorsImportToTableData = useCallback(
    data =>
      data.map(importError => {
        const { index, errors } = importError;

        const row = getTranslatedCadasterItems.map(item => {
          return {
            value: getSheetCellDataByCadasterItem(item.ref_code, index),
            error: errors
              ? !!errors.find(error => error.field === item.ref_code)
              : false,
            lineErrorIndex: index,
            refCode: item.ref_code,
          };
        });

        return row;
      }),
    [getSheetCellDataByCadasterItem, getTranslatedCadasterItems]
  );

  /** Convert all the errros messages and agglutinate into an string */
  const errorsImportMessageToString = useCallback(
    data =>
      data.map(importError => {
        const { errors } = importError;

        if (!errors) return null;

        const errorMessage = (
          <>
            {errors.map(error => {
              const { field, message } = error;

              const formmatedMessage = String(message).replace(field, "");

              return (
                <>
                  <strong>
                    {field === "password"
                      ? translate("common.password")
                      : getCadasterItemName(field)}
                  </strong>{" "}
                  {formmatedMessage}
                  <br />
                  <br />
                </>
              );
            })}
          </>
        );

        return errorMessage;
      }),
    [getCadasterItemName, translate]
  );

  /** Change status of an especific import, inside importLog */
  const updateImportLogStatus = useCallback(
    (importId, importStatus) => {
      const index = importLog.findIndex(i => Number(i.id) === Number(importId));
      const auxImportLog = importLog;

      if (index !== -1) {
        auxImportLog[index] = { ...auxImportLog[index], status: importStatus };
        setImportLog(auxImportLog);
      }
    },
    [importLog]
  );

  return (
    <ImportUsersContext.Provider
      value={{
        //* Functions
        setGroups,
        clearData,
        setTabIndex,
        importSheet,
        setSendEmail,
        setPasswordField,
        dataToBeImported,
        receiveSheetData,
        hasRelatedColumn,
        validateSheetImport,
        relateFieldToColumn,
        requiredCadasterItems,
        clearCorrectedErrors,
        updateSheetCellData,
        updateImportLogStatus,
        errorsImportToTableData,
        getCadasterItemByRefCode,
        errorsImportMessageToString,
        validateSheetImportSingleData,
        getSheetCellDataByCadasterItem,

        //* Datas
        sheetData,
        isImporting,
        setSheetFile,
        sheetColumns,
        sheetConfig,
        cadasterItems,
        importErrors,
        isValidating,
        validationFailed,
        validationStatus,
        VALIDATION_STATUS,
        tabIndex,
        rowLoading,
        sheetDataHasChanges,
        isImportErrorsChanged,
        importLog,
        setImportLog,
        importLogPagination,
        setImportLogPagination,
      }}
    >
      <ImportSocketProvider>{children}</ImportSocketProvider>
    </ImportUsersContext.Provider>
  );
};

ImportUsersProvider.propTypes = {
  children: PropTypes.element.isRequired,
};

const useImportUsers = () => {
  const context = useContext(ImportUsersContext);

  if (!context)
    throw new Error("useImportUsers must be within ImportUsersProvider.");

  return context;
};

export { ImportUsersProvider, useImportUsers, TABS_INDEX };
