import produce from "immer";

import { isDataValid } from "~/shared/utils/validations";
import types from "./types";

const INITIAL_STATE = {
  extraPlayers: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  extraButtons: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  extraEmbeds: {
    data: [],
    isLoaded: false,
    isCreating: null,
    isUpdating: null,
    isDeleting: null,
  },

  object: {
    data: {},
    isLoaded: false,
  },
};

export default function extraLinksReducer(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      // LIST EXTRA EMBED
      case types.EXTRA_EMBED.LIST.SUCCESSFUL: {
        const { extraEmbeds } = action.payload;

        if (isDataValid(extraEmbeds)) draft.extraEmbeds.data = extraEmbeds;

        draft.extraEmbeds.isLoaded = true;
        break;
      }

      // CREATE EXTRA EMEBED
      case types.EXTRA_EMBED.CREATE.REQUEST: {
        draft.extraEmbeds.isCreating = "PENDING";
        break;
      }
      case types.EXTRA_EMBED.CREATE.SUCCESSFUL: {
        const { extraEmbed } = action.payload;

        if (isDataValid(extraEmbed))
          draft.extraEmbeds.data = [...draft.extraEmbeds.data, extraEmbed];

        draft.extraEmbeds.isCreating = "FINISH";
        draft.extraEmbeds.isLoaded = true;
        break;
      }
      case types.EXTRA_EMBED.CREATE.FAILED: {
        draft.extraEmbeds.isCreating = "FAILED";
        break;
      }

      // UPDATE EXTRA EMBED
      case types.EXTRA_EMBED.UPDATE.REQUEST: {
        draft.extraEmbeds.isUpdating = "PENDING";
        break;
      }
      case types.EXTRA_EMBED.UPDATE.SUCCESSFUL: {
        const { extraEmbed, extraEmbedId } = action.payload;

        if (isDataValid(extraEmbed)) {
          const extraEmbeds = draft.extraEmbeds.data;

          const extraEmbedIndex = extraEmbeds.findIndex(
            obj => obj.id === extraEmbedId
          );

          extraEmbeds[extraEmbedIndex] = extraEmbed;

          draft.extraEmbeds.data = extraEmbeds;
        }

        draft.extraEmbeds.isUpdating = "FINISH";
        break;
      }
      case types.EXTRA_EMBED.UPDATE.FAILED: {
        draft.extraEmbeds.isUpdating = "FAILED";
        break;
      }

      // DELETE EXTRA EMBED
      case types.EXTRA_EMBED.DELETE.REQUEST: {
        draft.extraEmbeds.isDeleting = "PENDING";
        break;
      }
      case types.EXTRA_EMBED.DELETE.SUCCESSFUL: {
        const { extraEmbedId } = action.payload;

        const filteredArray = draft.extraEmbeds.data.filter(
          obj => obj.id !== extraEmbedId
        );

        draft.extraEmbeds.data = filteredArray;

        draft.extraEmbeds.isDeleting = "FINISH";
        break;
      }
      case types.EXTRA_EMBED.DELETE.FAILED: {
        draft.extraEmbeds.isDeleting = "FAILED";
        break;
      }

      // CLEAR EMBED
      case types.EXTRA_EMBED.CLEAR_EMBED: {
        draft.extraEmbeds.isCreating = null;
        draft.extraEmbeds.isDeleting = null;
        draft.extraEmbeds.isUpdating = null;

        break;
      }

      // CLEAR EXTRA EMBEDS
      case types.EXTRA_EMBED.CLEAR_LIST: {
        draft.extraEmbeds.data = [];
        draft.extraEmbeds.isLoaded = false;
        draft.extraEmbeds.isCreating = null;
        draft.extraEmbeds.isUpdating = null;
        draft.extraEmbeds.isDeleting = null;

        break;
      }

      // LIST EXTRA BUTTON
      case types.EXTRA_BUTTON.LIST.SUCCESSFUL: {
        const { extraLinks } = action.payload;

        if (isDataValid(extraLinks)) draft.extraButtons.data = extraLinks;

        draft.extraButtons.isLoaded = true;
        break;
      }

      // CREATE EXTRA BUTTON
      case types.EXTRA_BUTTON.CREATE.REQUEST: {
        draft.extraButtons.isCreating = "PENDING";
        break;
      }
      case types.EXTRA_BUTTON.CREATE.SUCCESSFUL: {
        const { extraLink } = action.payload;

        if (isDataValid(extraLink))
          draft.extraButtons.data = [...draft.extraButtons.data, extraLink];

        draft.extraButtons.isCreating = "FINISH";
        draft.extraButtons.isLoaded = true;
        break;
      }
      case types.EXTRA_BUTTON.CREATE.FAILED: {
        draft.extraButtons.isCreating = "FAILED";
        break;
      }

      // UPDATE EXTRA BUTTON
      case types.EXTRA_BUTTON.UPDATE.REQUEST: {
        draft.extraButtons.isUpdating = "PENDING";
        break;
      }
      case types.EXTRA_BUTTON.UPDATE.SUCCESSFUL: {
        const { extraLink, extraLinkId } = action.payload;

        if (isDataValid(extraLink)) {
          const extraButtons = draft.extraButtons.data;

          const extraButtonIndex = extraButtons.findIndex(
            obj => obj.id === extraLinkId
          );

          extraButtons[extraButtonIndex] = extraLink;

          draft.extraButtons.data = extraButtons;
        }

        draft.extraButtons.isUpdating = "FINISH";
        break;
      }
      case types.EXTRA_BUTTON.UPDATE.FAILED: {
        draft.extraButtons.isUpdating = "FAILED";
        break;
      }

      // DELETE EXTRA BUTTON
      case types.EXTRA_BUTTON.DELETE.REQUEST: {
        draft.extraButtons.isDeleting = "PENDING";
        break;
      }
      case types.EXTRA_BUTTON.DELETE.SUCCESSFUL: {
        const { extraLinkId } = action.payload;

        const filteredArray = draft.extraButtons.data.filter(
          obj => obj.id !== extraLinkId
        );

        draft.extraButtons.data = filteredArray;

        draft.extraButtons.isDeleting = "FINISH";
        break;
      }
      case types.EXTRA_BUTTON.DELETE.FAILED: {
        draft.extraButtons.isDeleting = "FAILED";
        break;
      }

      // CLEAR EXTRA BUTTONS
      case types.EXTRA_BUTTON.CLEAR_LIST: {
        draft.extraButtons.data = [];
        draft.extraButtons.isLoaded = false;
        draft.extraButtons.isCreating = null;
        draft.extraButtons.isUpdating = null;
        draft.extraButtons.isDeleting = null;

        break;
      }

      // LIST EXTRA BUTTON
      case types.EXTRA_PLAYER.LIST.SUCCESSFUL: {
        const { extraLinks } = action.payload;

        if (isDataValid(extraLinks)) draft.extraPlayers.data = extraLinks;

        draft.extraPlayers.isLoaded = true;
        break;
      }

      case types.EXTRA_PLAYER.CLEAR_LIST: {
        draft.extraPlayers.data = [];
        draft.extraPlayers.isLoaded = false;
        draft.extraPlayers.isCreating = null;
        draft.extraPlayers.isDeleting = null;
        draft.extraPlayers.isUpdating = null;

        break;
      }

      // CREATE EXTRA PLAYER
      case types.EXTRA_PLAYER.CREATE.REQUEST: {
        draft.extraPlayers.isCreating = "PENDING";
        break;
      }
      case types.EXTRA_PLAYER.CREATE.SUCCESSFUL: {
        const { extraLink } = action.payload;

        if (isDataValid(extraLink))
          draft.extraPlayers.data = [...draft.extraPlayers.data, extraLink];

        draft.extraPlayers.isCreating = "FINISH";
        draft.extraPlayers.isLoaded = true;
        break;
      }
      case types.EXTRA_PLAYER.CREATE.FAILED: {
        draft.extraPlayers.isCreating = "FAILED";
        break;
      }

      // UPDATE EXTRA PLAYER
      case types.EXTRA_PLAYER.UPDATE.REQUEST: {
        draft.extraPlayers.isUpdating = "PENDING";
        break;
      }
      case types.EXTRA_PLAYER.UPDATE.SUCCESSFUL: {
        const { extraLink, extraLinkId } = action.payload;

        if (isDataValid(extraLink)) {
          const extraPlayers = draft.extraPlayers.data;

          const extraButtonIndex = extraPlayers.findIndex(
            obj => obj.id === extraLinkId
          );

          extraPlayers[extraButtonIndex] = extraLink;

          draft.extraPlayers.data = extraPlayers;
        }

        draft.extraPlayers.isUpdating = "FINISH";
        draft.extraPlayers.isLoaded = false;
        draft.extraPlayers.isLoaded = true;
        break;
      }
      case types.EXTRA_PLAYER.UPDATE.FAILED: {
        draft.extraPlayers.isUpdating = "FAILED";
        break;
      }

      // DELETE EXTRA PLAYER
      case types.EXTRA_PLAYER.DELETE.REQUEST: {
        draft.extraPlayers.isDeleting = "PENDING";
        break;
      }
      case types.EXTRA_PLAYER.DELETE.SUCCESSFUL: {
        const { extraLinkId } = action.payload;

        const filteredArray = draft.extraPlayers.data.filter(
          obj => obj.id !== extraLinkId
        );

        draft.extraPlayers.data = filteredArray;

        draft.extraPlayers.isDeleting = "FINISH";
        break;
      }
      case types.EXTRA_PLAYER.DELETE.FAILED: {
        draft.extraPlayers.isDeleting = "FAILED";
        break;
      }

      // SIGN OUT
      case "@auth/SIGN_OUT": {
        draft.extraButtons.data = [];
        draft.extraButtons.isCreating = null;
        draft.extraButtons.isUpdating = null;
        draft.extraButtons.isDeleting = null;

        draft.extraPlayers.data = [];
        draft.extraPlayers.isCreating = null;
        draft.extraPlayers.isUpdating = null;
        draft.extraPlayers.isDeleting = null;

        draft.extraEmbeds.data = [];
        draft.extraEmbeds.isCreating = null;
        draft.extraEmbeds.isUpdating = null;
        draft.extraEmbeds.isDeleting = null;

        break;
      }
      default:
    }
  });
}
