// Function to check if list is not empty and not undefined or null
export const isListValid = list => {
  if (list !== undefined && list !== null) {
    if (list.length > 0) return true;

    return false;
  }

  return false;
};

// Function to check data is not undefined or null
export const isDataValid = data => {
  if (data !== null && data !== undefined) return true;

  return false;
};

// Function to check if an url has HTTP
export const hasHTTP = url => {
  if (isDataValid(url)) if (String(url).match("http")) return true;

  return false;
};

/** Verify if the arrays are equals */
export const isArraysEquals = (a, b) =>
  a.length === b.length && a.every((value, index) => value === b[index]);
