import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import broadcastRoomTypes from "./types";

import { showBroadcastRoomSuccessful } from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

export function* show({ payload }) {
  try {
    const { broadcastRoomId } = payload;

    const response = yield call(
      api.get,
      `admin/broadcastroom/${broadcastRoomId}`
    );

    if (validResponseStatus(response.status)) {
      const broadcastRoom = response.data;

      yield put(showBroadcastRoomSuccessful(broadcastRoom));
    }
  } catch (err) {
    console.log(err);
  }
}

export default all([takeLatest(broadcastRoomTypes.show.REQUEST, show)]);
