import React from "react";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { RankingTable } from "~/shared/components/organisms";
import { Icon } from "~/shared/components/atoms";
import { Pagination, SearchBar } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";
import { useAdminGamification } from "../../hooks";
import { API_STATUS } from "~/shared/utils/apis";
import { openAdminGamificationUserExtract } from "~/shared/routes/navigation";
import { EditUserScoreModal } from "../EditUserScoreModal";
import { ResetUserScoreModal } from "../ResetUserScoreModal";

const AdminRankingTable = () => {
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();
  const { listRanking, ranking, rankingApiStatus } = useAdminGamification();

  //* STATES
  const [search, setSearch] = React.useState("");
  const [showEditScoreModal, setShowEditScoreModal] = React.useState(false);
  const [showResetScoreModal, setShowResetScoreModal] = React.useState(false);
  const [userRanking, setUserRanking] = React.useState(null);

  //* FUNCTIONS
  React.useEffect(() => {
    if (!ranking.isLoaded) listRanking(1, "");
  }, [listRanking, ranking.isLoaded]);

  const openEditScoreModal = userRankingData => {
    setUserRanking(userRankingData);
    setShowEditScoreModal(true);
  };

  const openResetScoreModal = userRankingData => {
    setUserRanking(userRankingData);
    setShowResetScoreModal(true);
  };

  /** This funnction will check wich option was clicked based on it Id,
   * then trigerer the respective function.
   */
  const handleOnOptionClick = (userRankingData, option) => {
    const { user_id: userId } = userRankingData;

    switch (option.id) {
      case 1:
        openAdminGamificationUserExtract({ userId });
        break;

      case 2:
        openEditScoreModal(userRankingData);

        break;

      case 3:
        openResetScoreModal(userRankingData);
        break;

      default:
        break;
    }
  };

  const handleOnModalClose = callback => {
    callback();
    setUserRanking(null);
  };

  return (
    <div>
      <SearchBar
        placeholder={translate("entities.users.search")}
        style={{ maxWidth: 300, marginBottom: 10 }}
        onChange={value => {
          setSearch(value);
          listRanking(ranking.page, value);
        }}
      />
      <RankingTable
        containerVariant="contained"
        onUserNameClick={userRankingData => openEditScoreModal(userRankingData)}
        isLoading={rankingApiStatus === API_STATUS.PAGING || !ranking.isLoaded}
        loaderListSize={10}
        data={ranking.data.map(rank => ({
          ...rank,
          avatar: rank.avatar,
          userName: rank.name,
          points: rank.points,
          position: rank.rank_position,
        }))}
        options={[
          { id: 1, name: translate("entities.gamification.seePointsExtract") },
          { id: 2, name: translate("entities.gamification.editScore") },
          {
            id: 3,
            name: translate("entities.gamification.resetScore"),
            color: colors.redWrong,
          },
        ]}
        onOptionClick={handleOnOptionClick}
        style={{ minWidth: 400 }}
        empty={{
          search,
          variant: search && "search",
          message: translate("entities.gamification.empty.message"),
          description: translate("entities.gamification.empty.description"),
          icon: <Icon iconName="gamification" />,
          style: { justifyContent: "flexStart" },
        }}
      />

      {ranking.data.length > 0 && (
        <Pagination
          currentPage={ranking.page}
          lastPage={ranking.lastPage}
          handlePagination={page => listRanking(page)}
          style={{ marginTop: 20 }}
        />
      )}

      {userRanking && (
        <EditUserScoreModal
          show={showEditScoreModal}
          userRanking={userRanking}
          onClose={() => handleOnModalClose(() => setShowEditScoreModal(false))}
          onCancel={() =>
            handleOnModalClose(() => setShowEditScoreModal(false))
          }
        />
      )}
      {userRanking && (
        <ResetUserScoreModal
          show={showResetScoreModal}
          userRanking={userRanking}
          onClose={() =>
            handleOnModalClose(() => setShowResetScoreModal(false))
          }
          onCancel={() =>
            handleOnModalClose(() => setShowResetScoreModal(false))
          }
        />
      )}
    </div>
  );
};

export { AdminRankingTable };
