import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listAdminRegistrationFieldsSuccessful,
  deleteRegistrationFieldFailed,
  deleteRegistrationFieldSuccessful,
  createRegistrationFieldFailed,
  createRegistrationFieldSuccessful,
  clearRegistrationFieldForm,
  updateRegistrationFieldFailed,
  updateRegistrationFieldSuccessful,
  reorderRegistrationFieldFailed,
  reorderRegistrationFieldSuccessful,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/admin/cadaster_item");

    if (validResponseStatus(response.status)) {
      const registrationFields = response.data;

      yield put(listAdminRegistrationFieldsSuccessful({ registrationFields }));
    }
  } catch (err) {
    console.log(err);
  }
}

// CRAETE
export function* create({ payload }) {
  try {
    const { registrationField } = payload;

    const response = yield call(
      api.post,
      `/admin/cadaster_item/`,
      registrationField
    );

    if (validResponseStatus(response.status)) {
      const newRegistrationField = response.data;

      yield put(
        createRegistrationFieldSuccessful({
          registrationField: newRegistrationField,
        })
      );
    }

    yield put(clearRegistrationFieldForm());
  } catch (err) {
    yield put(createRegistrationFieldFailed());
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { registrationField, registrationFieldId } = payload;

    const response = yield call(
      api.put,
      `/admin/cadaster_item/${registrationFieldId}`,
      registrationField
    );

    if (validResponseStatus(response.status)) {
      const newRegistrationField = response.data;

      yield put(
        updateRegistrationFieldSuccessful({
          registrationField: newRegistrationField,
        })
      );
    }

    yield put(clearRegistrationFieldForm());
  } catch (err) {
    yield put(updateRegistrationFieldFailed());
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { registrationFieldId } = payload;

    const response = yield call(
      api.delete,
      `/admin/cadaster_item/${registrationFieldId}`
    );

    if (validResponseStatus(response.status)) {
      yield put(
        deleteRegistrationFieldSuccessful({
          registrationFieldId,
        })
      );
    }
    yield put(clearRegistrationFieldForm());
  } catch (err) {
    yield put(deleteRegistrationFieldFailed());
  }
}

// REORDER
export function* reorder({ payload }) {
  try {
    const { reorderedList } = payload;

    const response = yield call(api.put, `/admin/order/cadaster_item/`, {
      data: reorderedList,
    });

    if (validResponseStatus(response.status)) {
      yield put(reorderRegistrationFieldSuccessful());
    }

    yield put(clearRegistrationFieldForm());
  } catch (err) {
    yield put(reorderRegistrationFieldFailed());
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
  takeLatest(types.REORDER.REQUEST, reorder),
]);
