import React from "react";
import PropTypes from "prop-types";
import { lighten } from "polished";
import { ThemeContext } from "styled-components";

import { FlexibleWidthXYPlot, VerticalBarSeries } from "react-vis";

//* CUSTOM IMPORTS
import { getFullHours, getFullDate } from "../../../utils/dates";
import { Card, Typography } from "../../atoms";

const CardTip = ({ date, counter, xPosition, granularity, tipDescription }) => {
  //* FUNCTIONS
  const renderDataTime = () => {
    switch (granularity) {
      case "hour":
        return `Hoje às ${getFullHours(date, 3)}`;

      case "day":
        return getFullDate(date, true, true);

      case "minute":
        return `${getFullDate(date, true, true)} às ${getFullHours(date)}`;

      default:
        return null;
    }
  };

  return (
    <Card
      style={{
        position: "absolute",
        left: xPosition + 115,
        bottom: -10,
        zIndex: 1,
        display: "flex",
        flexDirection: "column",
        minWidth: 120,
      }}
    >
      <Typography
        maxLines={2}
        className="tiny opaque"
        style={{ marginBottom: 10 }}
      >
        {renderDataTime()}
      </Typography>
      <div>
        <Typography className="bigTitle normal bold">{counter}</Typography>
        <Typography className="uppercase small bold opaque">
          {tipDescription}
        </Typography>
      </div>
    </Card>
  );
};

CardTip.propTypes = {
  counter: PropTypes.number.isRequired,
  xPosition: PropTypes.number.isRequired,
  date: PropTypes.string.isRequired,
  granularity: PropTypes.string.isRequired,
  tipDescription: PropTypes.string.isRequired,
};

const INITIAL_VALUE = {
  x: null,
  y: null,
  date: null,
  xPosition: null,
  index: null,
};

const barChartPropTypes = {
  chartData: PropTypes.arrayOf(
    PropTypes.shape({
      count: PropTypes.string,
      date: PropTypes.string,
    })
  ).isRequired,

  /** Define the granularity to be displayed in the <b>BarChart</b>. default is <b>hour</b>
   *
   * Example: If the <b>granularity</b> is <b>day</b>, the
   * <b>BarChart</b> will display the candles in <b>day</b>
   */
  granularity: PropTypes.oneOf(["hour", "day"]),

  /** Variant control the BarChart pallette colors, default is <b>accent</b> */
  variant: PropTypes.oneOf(["white", "accent"]),

  /** Control the description of the card that appears when user hover an candle */
  tipDescription: PropTypes.string,
};

const barChartDefaultProps = {
  variant: "accent",
  granularity: "hour",
  tipDescription: null,
};

/** BarChart is commonly used in the Dahboard */
function BarChart({ variant, chartData, granularity, tipDescription }) {
  const { colors } = React.useContext(ThemeContext);
  const whiteTheme = variant === "white";

  //* STATES
  const [dataPoint, setDataPoint] = React.useState(INITIAL_VALUE);

  //* FUNCTIONS
  const isCartTipVisible = dataPoint.xPosition !== null;

  const setBarColor = index => {
    if (dataPoint.index === index) return 1;

    if (isCartTipVisible) return 2;

    return 0;
  };

  const data = chartData
    .map((d, index) => {
      return { x: index, y: Number(d.count), date: d.date };
    })
    .map((d, i) => ({
      ...d,
      color: setBarColor(i),
    }));

  const barNormalColor = whiteTheme
    ? "#ffffff80"
    : lighten(0.15, colors.accent);
  const barSelectedColor = whiteTheme ? "#fff" : colors.accent;
  const barOpaqueColor = whiteTheme ? "#ffffff40" : lighten(0.3, colors.accent);

  return (
    <div
      style={{
        height: 41,
        borderBottom: `1px solid ${barNormalColor}`,
      }}
    >
      <FlexibleWidthXYPlot
        colorDomain={[0, 1, 2]}
        height={80}
        xType="time"
        onMouseLeave={() => setDataPoint(INITIAL_VALUE)}
        style={{ cursor: "pointer" }}
      >
        <VerticalBarSeries
          colorRange={[barNormalColor, barSelectedColor, barOpaqueColor]}
          data={data}
          onNearestX={(datapoint, { index, innerX }) => {
            if (dataPoint.xPosition !== innerX)
              setDataPoint({
                x: datapoint.x,
                y: datapoint.y,
                date: datapoint.date,
                index,
                xPosition: innerX,
              });
          }}
        />

        {isCartTipVisible && (
          <CardTip
            granularity={granularity}
            time="2 horas atrás"
            xPosition={dataPoint.xPosition}
            counter={dataPoint.y}
            date={dataPoint.date}
            tipDescription={tipDescription}
          />
        )}
      </FlexibleWidthXYPlot>
    </div>
  );
}

BarChart.propTypes = barChartPropTypes;

BarChart.defaultProps = barChartDefaultProps;

export { BarChart, barChartPropTypes, barChartDefaultProps };
