import React from "react";
import PropTypes from "prop-types";

//* APIs
import { useProfile } from "~/modules/signed/modules/client/pages/Profile/hooks";
import { useBookmarkProfile } from "~/modules/signed/modules/client/pages/Profile/hooks/bookmarks";

//* CUSTOM IMPORTS
import { SearchBar } from "../../styles";
import { Icon } from "~/shared/components/atoms";
import { CellList, Pagination, Empty } from "~/shared/components/molecules";
import { UserBroadcastRoomCell } from "~/modules/signed/modules/client/components/cells/UserBroadcastRoomCell";
import NetworkCellLoader from "~/shared/components/molecules/cellLoaders/NetworkCellLoader";
import { useTranslation } from "~/shared/hooks/translation";
import { BOOKMARK_TYPES } from "~/shared/utils/types";
import { openBroadcastRoom, openMarketRoom } from "~/shared/routes/navigation";
import { isListValid } from "~/shared/utils/validations";

function BroadcastRooms({ isFetcheable }) {
  const { isUserLoaded } = useProfile();
  const {
    bookmarkRooms,
    roomsCurrentPage,
    searchString,
    isSearching,
    paginateBookmarks,
    listBookmarks,
    setBookmarkRooms,
    setSearchString,
  } = useBookmarkProfile();
  const { translate } = useTranslation();

  //* FUNCTIONS
  React.useEffect(() => {
    if (!bookmarkRooms.isLoaded && isFetcheable)
      listBookmarks(bookmarkRooms.page, "broadcast_rooms", setBookmarkRooms);
  }, [
    bookmarkRooms.isLoaded,
    bookmarkRooms.page,
    isFetcheable,
    listBookmarks,
    setBookmarkRooms,
  ]);

  const handleEmptyMessage = () => {
    if (searchString === "") {
      return (
        <Empty
          message={translate("entities.profile.body.bookmark.emptyMessage")}
          description={translate(
            "entities.profile.body.bookmark.emptyDescription"
          )}
          icon={<Icon iconName="broadcastRooms" />}
        />
      );
    }
    return (
      <Empty
        message={translate(
          "entities.profile.body.connection.emptySearchMessage"
        )}
        description={translate(
          "entities.profile.body.connection.emptySearchDescription"
        )}
        icon={<Icon iconName="broadcastRooms" />}
      />
    );
  };

  const renderCell = broadcastRoom => {
    const {
      id,
      room,
      __meta__,
      open_live: openLive,
      end_live: endLive,
      on_live: onLive,
    } = broadcastRoom;

    return (
      <UserBroadcastRoomCell
        isAdmin
        id={room.id}
        isBookmarked={room.bookmark}
        image={room.logo}
        title={room.title}
        tags={room.tags}
        bookmark={room.bookmark}
        speakers={__meta__.speakers}
        startDate={openLive}
        endDate={endLive}
        onClick={() =>
          openBroadcastRoom({ broadcastRoomId: id, title: room.title })
        }
        bottomTags={{
          tags:
            isListValid(room.partners) &&
            room.partners.map(partner => ({
              name: partner.title,
              onClick: () => openMarketRoom(partner.id, partner.title),
              hoverable: true,
            })),
        }}
        onLive={onLive}
      />
    );
  };
  return (
    <div>
      <SearchBar
        placeholder={translate("common.search")}
        onChange={value => {
          setSearchString(value);

          listBookmarks(
            bookmarkRooms.page,
            "broadcast_rooms",
            setBookmarkRooms,
            value
          );
        }}
      />

      <CellList
        isLoaded={isUserLoaded && !bookmarkRooms.isPaging && !isSearching}
        renderCellLoader={() => <NetworkCellLoader />}
        loaderListSize={3}
        gap={20}
        emptyComponent={handleEmptyMessage()}
        data={bookmarkRooms.data}
        renderCell={room => renderCell(room)}
      />

      <Pagination
        currentPage={roomsCurrentPage}
        handlePagination={page => {
          paginateBookmarks(BOOKMARK_TYPES.ROOMS, page);
        }}
        lastPage={bookmarkRooms.lastPage}
      />
    </div>
  );
}

BroadcastRooms.propTypes = {
  /** If <b>true</b> this component will fetch the API */
  isFetcheable: PropTypes.bool.isRequired,
};

export { BroadcastRooms };
