import React from "react";
import { useSelector, useDispatch } from "react-redux";

//* ACTIONS
import { receiveLiveBroadcastRooms } from "~/modules/signed/modules/client/store/broadcastRooms/actions";

//* APIs
import { listBroadcastRooms } from "~/modules/signed/modules/client/apis/broadcastRooms";

//* CUSTOM IMPORTS
import { usePip } from "~/modules/signed/hooks/pip";
import { openBroadcastRoom } from "~/shared/routes/navigation";
import { Typography, Box } from "~/shared/components/atoms";
import LiveBroadcastRoomCell from "~/shared/components/organisms/cells/LiveBroadcastRoomCell";
import { CellSlider, SlapHole } from "./styles";
import { isListValid } from "~/shared/utils/validations";

function LiveBroadcastRoomSlider() {
  const dispatch = useDispatch();
  const { data: liveRooms, isLoaded } = useSelector(
    state => state.broadcastRooms.liveRooms
  );

  //* STATES
  const { isPipVisible } = usePip();
  const [checkedSlideIndex, setCheckedSlideIndex] = React.useState(0);

  //* FUNCTIONS
  React.useEffect(() => {
    const list = async () => {
      await listBroadcastRooms({ onLive: true, search: "" }).then(response => {
        let auxArray = response.data.data;

        //* *If array has only 2 rooms on live, duplicate the array to fill the
        //* *CellSlider component */
        if (auxArray.length === 2)
          auxArray = [...response.data.data, ...response.data.data];

        dispatch(
          receiveLiveBroadcastRooms({
            broadcastRooms: { ...response.data, data: auxArray },
          })
        );
      });
    };

    list();

    document.body.style.overflowX = "hidden";

    return () => {
      document.body.style.overflowX = "auto";
    };
  }, [dispatch]);

  return (
    isLoaded &&
    isListValid(liveRooms.data) && (
      <Box>
        <Typography
          variant="contained"
          className="bigTitle bold normal centered"
          style={{ marginBottom: 20 }}
        >
          Ao vivo agora
        </Typography>

        <CellSlider
          gap={20}
          heigth={270}
          data={liveRooms.data}
          checked={checkedSlideIndex}
          onChange={index => setCheckedSlideIndex(index)}
          renderCell={(liveRoom, index) => {
            const {
              id,
              room,
              __meta__,
              open_live: openLive,
              end_live: endLive,
              video_player: videoPlayer,
              type,
              on_live: onLive,
            } = liveRoom;

            return (
              <LiveBroadcastRoomCell
                isLoading={!isLoaded}
                onClick={() =>
                  openBroadcastRoom({ broadcastRoomId: id, title: room.title })
                }
                videoPlayer={{
                  src: videoPlayer,
                  muted: true,
                  type,
                  plyrId: `liveBroadcastRoom${id}-${index}`,
                  //* *Render an thumbnail if slider is not in center, if is in
                  //* * center render the player */
                  thumbnail: room.logo,
                  showThumbnail: checkedSlideIndex !== index,
                  onLive,

                  //* *Disable autoPlay if Pip is disabled */
                  autoPlay: !isPipVisible,
                }}
                broadcastRoomCell={{
                  title: room.title,
                  tags: room.tags,
                  startDate: openLive,
                  endDate: endLive,
                  bottomTags: {
                    trim: true,
                    tags:
                      isListValid(room.partners) &&
                      room.partners.map(partner => ({
                        name: partner.title,
                      })),
                  },
                  image: room.logo,
                  speakers: __meta__.speakers,
                }}
              />
            );
          }}
        />

        <SlapHole />
      </Box>
    )
  );
}

export default LiveBroadcastRoomSlider;
