import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Typography } from "../../../atoms";
import { CRUDOptionsMenu } from "../../CRUDOptionsMenu";

//* STYLES
import { Container, IFrame } from "./styles";

const extraEmbedCellPropTypes = {
  title: PropTypes.string,

  /** The Url for the <b>IFrame</b>. */
  src: PropTypes.string,

  /** Callback that is triggered when users click into <b>EditButton</b> */
  onEdit: PropTypes.func,

  /** Callback that is triggered when users click into <b>DeletButton</b> */
  onDelete: PropTypes.func,
};

const extraEmbedCellDefaultProps = {
  title: null,
  src: null,
  onEdit: () => {},
  onDelete: () => {},
};

const ExtraEmbedCell = ({ title, src, onDelete, onEdit, ...rest }) => {
  //* FUNCTIONS
  function validateVideoSrc(videoSource) {
    if (String(videoSource).match("http")) return true;

    return false;
  }

  return (
    <Container {...rest}>
      {title && (
        <Typography
          maxLines={1}
          style={{ marginBottom: 8 }}
          className="bold normal uppercase small"
        >
          {title}
        </Typography>
      )}

      {validateVideoSrc(src) && (
        <IFrame
          id="iframe-fragment"
          allowfullscreen
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; fullscreen; microphone; camera; gyroscope; picture-in-picture"
          src={src}
        />
      )}

      <CRUDOptionsMenu
        className="extra-embed-option"
        entity="embed"
        entityName={title}
        onDelete={onDelete}
        onEdit={onEdit}
      />
    </Container>
  );
};

ExtraEmbedCell.propTypes = extraEmbedCellPropTypes;

ExtraEmbedCell.defaultProps = extraEmbedCellDefaultProps;

export { ExtraEmbedCell, extraEmbedCellDefaultProps, extraEmbedCellPropTypes };
