import React from "react";
import PropTypes from "prop-types";
import { MdEmail } from "react-icons/md";

// CUSTOM IMPORTS
import { AdminFileInput } from "~/modules/signed/modules/cms/components/AdminFileInput";
import {
  ScrollContainer,
  TextEditor,
  LeftContainer,
  RigthContainer,
} from "./styles";
import { IconedButton } from "~/shared/components/atoms";
import EmailPreviewModal from "./EmailPreview";
import { useTranslation } from "~/shared/hooks/translation";
import { useDynamicTranslation } from "~/modules/signed/hooks/dynamicTranslation";

function EmailAspect({
  onTextEditorChange,
  onHeaderChange,
  email,
  bodyValue,
  headerValue,
}) {
  const { translate } = useTranslation();
  const { currentLanguage } = useDynamicTranslation();

  // STATES
  const [textEditorLang, setTextLangEditor] = React.useState(null);
  const [showPreview, setShowPreview] = React.useState(false);

  // FUNCTIONS
  React.useEffect(() => setTextLangEditor(currentLanguage), [currentLanguage]);

  const initialHeaderData = () => {
    if (email) {
      onHeaderChange(email.header_url);
      return email.header_url;
    }
    return null;
  };

  return (
    <ScrollContainer>
      <LeftContainer>
        <IconedButton
          icon={<MdEmail />}
          placeholder={translate("entities.emails.view")}
          onClick={() => setShowPreview(true)}
        />

        <TextEditor
          name="text"
          variables={[
            { id: 1, name: "CPF", value: "CPF" },
            { id: 2, name: `${translate("common.name")}`, value: "NOME" },
            { id: 3, name: "Email", value: "EMAIL" },
            {
              id: 4,
              name: `${translate("common.eventName")}`,
              value: "NOME_EVENTO",
            },
          ]}
          onLanguageChange={lang => setTextLangEditor(lang)}
          onChange={value => onTextEditorChange(value)}
        />
      </LeftContainer>

      <RigthContainer>
        <AdminFileInput
          title={translate("entities.emails.header")}
          name="header"
          heigth={120}
          width={600}
          style={{ marginBottom: 16 }}
          onUploadFinish={url => onHeaderChange(url)}
          initialValue={initialHeaderData}
        />
      </RigthContainer>

      {showPreview && (
        <EmailPreviewModal
          show={showPreview}
          onClose={() => setShowPreview(false)}
          text={bodyValue}
          currentLanguage={textEditorLang && textEditorLang.refCode}
          header_url={headerValue}
        />
      )}
    </ScrollContainer>
  );
}

EmailAspect.propTypes = {
  onTextEditorChange: PropTypes.func.isRequired,
  onHeaderChange: PropTypes.func.isRequired,
  email: PropTypes.object,
  bodyValue: PropTypes.string,
  headerValue: PropTypes.string,
};

EmailAspect.defaultProps = {
  email: null,
  bodyValue: null,
  headerValue: null,
};

export default EmailAspect;
