import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Box, Typography, avatarPropTypes } from "~/shared/components/atoms";
import { Infos } from "~/shared/components/molecules";
import Loader from "~/shared/components/molecules/cellLoaders/CheckListCellLoader";
import { useGamification } from "~/modules/signed/hooks/gamification";

const infoGamificationPropTypes = {
  position: PropTypes.number.isRequired,
  userName: PropTypes.string.isRequired,
  points: PropTypes.number.isRequired,
  avatar: PropTypes.shape(avatarPropTypes),
  isLoading: PropTypes.bool,
};

const infoGamificationDefaultProps = {
  isLoading: false,
  avatar: null,
};

const GamificationInfos = ({
  position,
  userName,
  points,
  avatar,
  isLoading,
}) => {
  const { renderGamificationFrame } = useGamification();

  if (isLoading)
    return (
      <Loader
        avatarSize={68}
        height={68}
        width={400}
        infosX={80}
        style={{ marginBottom: 40 }}
      />
    );

  return (
    <Box display="flex" alignItems="center" marginBottom="44px">
      <Typography className="subTitle bold accent" style={{ marginRight: 16 }}>
        {position}º
      </Typography>

      <Infos
        avatar={{ ...avatar, frame: renderGamificationFrame(position) }}
        title={userName}
        subTitle={`${points}pts`}
        titleStyles="subTitle bold accent"
        subTitleStyles="regular opaque"
      />
    </Box>
  );
};

GamificationInfos.propTypes = infoGamificationPropTypes;

GamificationInfos.defaultProps = infoGamificationDefaultProps;

export {
  GamificationInfos,
  infoGamificationPropTypes,
  infoGamificationDefaultProps,
};
