/* eslint-disable prefer-promise-reject-errors */
import XLSX from "sheetjs-style";

//* CUSTOM IMPORTS
import i18n from "~/shared/translation/i18n";
import { isListValid } from "~/shared/utils/validations";
import { toSnakeCase } from "~/shared/utils/strings";

export const sheetToJson = ({ sheet, maxLines = null }) =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onabort = () => reject();

    reader.onerror = () => reject();

    reader.onload = () => {
      const data = new Uint8Array(reader.result);
      const workbook = XLSX.read(data, { type: "array", cellDates: true });
      const firstSheet = workbook.Sheets[workbook.SheetNames[0]];

      const jsonArray = XLSX.utils.sheet_to_json(firstSheet, {
        defval: "",
        dateNF: "dd/MM/yyyy",

        //* *Formmat headers to lower case and remove spaces */
      });

      //* *If sheet is empty throw an error */
      if (!isListValid(jsonArray)) {
        reject(i18n.t("entities.spreadsheet.isEmpty"));
      } else {
        /** If maxlines has data, add an validation if the spreadsheet has more than
         * maxLines trow an error
         */
        if (maxLines) {
          if (jsonArray.length > maxLines + 1) {
            reject(`
              ${i18n.t("entities.spreadsheet.exceededLines")}.
              ${i18n.t("entities.spreadsheet.maxLinesAllowed.part1")}
              ${maxLines}
              ${i18n.t("entities.spreadsheet.maxLinesAllowed.part2")}
              `);
          }
        }

        const formmatedArray = jsonArray.map(obj => {
          const formmatedObject = {};

          //* * Formmat all property name of the current object*/
          Object.keys(obj).forEach(attr => {
            formmatedObject[toSnakeCase(attr)] = obj[attr];
          });

          return formmatedObject;
        });

        //* *Get the first item of the formatted array and return only the properties  */
        const headers = Object.keys(formmatedArray[0]).map(key => key);

        resolve({
          data: {
            headers,
            jsonArray: formmatedArray,
          },
        });
      }
    };
    reader.readAsArrayBuffer(sheet);
  });
