import React from "react";
import PropTypes from "prop-types";
import { MdSearch } from "react-icons/md";

//* APIs
import { listSchedules } from "~/modules/signed/modules/client/apis/schedules";

//* CUSTOM IMPORTS
import { Trans } from "~/shared/components/atoms";
import { ScheduleUserTabList } from "../../../../../../components/lists/ScheduleUserTabList";
import { REQUEST_STATUS } from "~/shared/utils/hooks/useApi";

const propTypes = {
  search: PropTypes.string.isRequired,
  categoryId: PropTypes.number.isRequired,
  selectedSchedule: PropTypes.func.isRequired,
  setSelectedSchedule: PropTypes.func.isRequired,
};

const ScheduleList = ({
  search,
  categoryId,
  selectedSchedule,
  setSelectedSchedule,
  ...rest
}) => {
  //* STATES
  const [isLoading, setIsLoading] = React.useState(REQUEST_STATUS.IDLE);
  const [schedule, setSchedule] = React.useState([]);

  //* FUNCTIONS
  React.useEffect(() => {
    setSelectedSchedule(null);
    setIsLoading(REQUEST_STATUS.PENDING);

    const list = async () => {
      await listSchedules({ categoryId, search }).then(response => {
        setSchedule(response.data);
        setIsLoading(REQUEST_STATUS.RESOLVED);
      });
    };

    list();
  }, [categoryId, search, setSelectedSchedule]);

  return (
    <ScheduleUserTabList
      data={schedule}
      isLoaded={isLoading === REQUEST_STATUS.RESOLVED}
      selectedCellId={selectedSchedule && selectedSchedule.id}
      empty={
        search !== "" && {
          message: (
            <Trans translationKey="pages.homeSchedule.emptySearch.message">
              Nenhum resultado encontrado
            </Trans>
          ),
          description: (
            <>
              <Trans translationKey="pages.homeSchedule.emptySearch.description">
                Não encontramos programações relacionadas a{" "}
              </Trans>

              <strong>{`"${search}"`}</strong>
            </>
          ),
          icon: <MdSearch />,
        }
      }
      {...rest}
    />
  );
};

ScheduleList.propTypes = propTypes;

export default ScheduleList;
