import React from "react";
import PropTypes from "prop-types";

// CUSTOM IMPORTS
import { getFullHours, getFullDate } from "~/shared/utils/dates";
import { toHtml } from "~/shared/utils/strings";
import { setIcon } from "~/shared/utils/icons";
import {
  Container,
  Icon,
  Line,
  Title,
  Description,
  Times,
  Time,
} from "./styles";

function ActivityCell({ className, activity, noLine }) {
  const { title, created_at: createdAt, icon, description } = activity;

  return (
    <Container className={className}>
      <div id="activity-cell-left">
        <Icon color={icon.color}>{setIcon(icon.type)}</Icon>

        {!noLine && <Line />}
      </div>

      <div id="activity-cell-rigth">
        <Title>{title}</Title>

        <Description
          dangerouslySetInnerHTML={toHtml({ string: description })}
        />
      </div>

      <Times>
        <Time>{getFullDate(createdAt)}</Time>
        <Time>{getFullHours(createdAt, 3)}</Time>
      </Times>
    </Container>
  );
}

ActivityCell.propTypes = {
  activity: PropTypes.object.isRequired,
  className: PropTypes.string,
};

ActivityCell.defaultProps = {
  className: null,
};

export default ActivityCell;
