import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listBannersSuccessful,
  showBannerSuccessful,
  deleteBannerFailed,
  deleteBannerSuccessful,
  createBannerFailed,
  createBannerSuccessful,
  clearEvents,
  updateBannerFailed,
  updateBannerSuccessful,
  reorderBannersFailed,
  reorderBannersSuccessful,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, "/admin/banner");

    if (validResponseStatus(response.status)) {
      const banners = response.data;

      yield put(listBannersSuccessful({ banners }));
    }
  } catch (err) {
    console.log(err);
  }
}

// SHOW
export function* show({ payload }) {
  const { bannerId } = payload;

  try {
    const response = yield call(api.get, `/admin/banner/${bannerId}`);

    if (validResponseStatus(response.status)) {
      const banner = response.data;

      yield put(showBannerSuccessful({ banner }));
    }
  } catch (err) {
    console.log(err);
  }
}

// CRAETE
export function* create({ payload }) {
  try {
    const { bannerData } = payload;

    const response = yield call(api.post, `/admin/banner/`, bannerData);

    if (validResponseStatus(response.status)) {
      const banner = response.data;

      yield put(
        createBannerSuccessful({
          banner,
        })
      );
    }

    yield put(clearEvents());
  } catch (err) {
    console.log(err);
    yield put(createBannerFailed());
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { bannerData, bannerId } = payload;

    const response = yield call(
      api.put,
      `/admin/banner/${bannerId}`,
      bannerData
    );

    if (validResponseStatus(response.status)) {
      const banner = response.data;

      yield put(
        updateBannerSuccessful({
          banner,
        })
      );
    }

    yield put(clearEvents());
  } catch (err) {
    console.log(err);
    yield put(updateBannerFailed());
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { bannerId } = payload;

    const response = yield call(api.delete, `/admin/banner/${bannerId}`);

    if (validResponseStatus(response.status)) {
      yield put(
        deleteBannerSuccessful({
          bannerId,
        })
      );
    }
    yield put(clearEvents());
  } catch (err) {
    console.log(err);
    yield put(deleteBannerFailed());
  }
}

// REORDER
export function* reorder({ payload }) {
  try {
    const { reorderedList } = payload;

    const response = yield call(api.put, `/admin/order/banner/`, {
      data: reorderedList,
    });

    if (validResponseStatus(response.status)) {
      yield put(reorderBannersSuccessful());
    }

    yield put(clearEvents());
  } catch (err) {
    console.log(err);
    yield put(reorderBannersFailed());
  }
}

export default all([
  takeLatest(types.LIST.REQUEST, list),
  takeLatest(types.SHOW.REQUEST, show),
  takeLatest(types.CREATE.REQUEST, create),
  takeLatest(types.UPDATE.REQUEST, update),
  takeLatest(types.DELETE.REQUEST, destroy),
  takeLatest(types.REORDER.REQUEST, reorder),
]);
