import React from "react";

//* CUSTOM IMPORTS
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { useTranslation } from "~/shared/hooks/translation";

//* TABS
import { StaticTabs } from "~/shared/components/molecules";
import { GeneralTab } from "./components/GeneralTab";
import { BannersTab } from "./components/BannersTab";
import { SupportTab } from "./components/SupportTab";
import { EmailTab } from "./components/EmailTab";

function SettingsTabs() {
  const { scrollToTop } = useCMS();
  const { translate } = useTranslation();

  //* FUNCTIONS
  const handleOnChange = () => scrollToTop();

  // TABS
  const tabs = [
    { id: 1, name: translate("common.general") },
    {
      id: 2,
      name: translate("entities.banners.rotating"),
    },
    { id: 3, name: translate("common.support") },
    { id: 4, name: translate("common.email") },
  ];

  return (
    <StaticTabs tabs={tabs} onChange={handleOnChange}>
      <GeneralTab />

      <BannersTab />

      <SupportTab />

      <EmailTab />
    </StaticTabs>
  );
}

export default SettingsTabs;
