import React from "react";
import { useParams } from "react-router-dom";

//* CUSTOM IMPORTS
import { NOTIFICATION_TYPES } from "~/shared/utils/types";
import { NotificationDetailsFragment } from "../../components/NotificationDetailsFragment";
import { useTranslation } from "~/shared/hooks/translation";

const NotificationReceived = () => {
  const { translate } = useTranslation();

  const { notificationId } = useParams();

  return (
    <NotificationDetailsFragment
      notificationId={notificationId}
      action={NOTIFICATION_TYPES.RECEIVED}
      title={translate("entities.notifications.received.plural")}
      emptyMessage={translate("entities.notifications.received.empty.message")}
      emptyDescription={translate(
        "entities.notifications.received.empty.description"
      )}
      countDescription={translate("common.peopleWhoReceived")}
      barChartTipDescription={translate("common.receipts")}
    />
  );
};

export default NotificationReceived;
