import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import ReportItemLoader from "../cellLoaders/FieldsetLoader";
import {
  Card,
  CardDot,
  ReportItems,
  CounterFieldset,
  Title,
  Description,
  Counter,
  DownloadButton,
} from "./styles";

const dashboardReportCardPropTypes = {
  /** Determines the Dot Color */
  dotColor: PropTypes.string,

  /** Render an Title in the card */
  title: PropTypes.string.isRequired,

  /** Object that contains report items  */
  reportItems: PropTypes.arrayOf(PropTypes.object),

  /** Determines the status of the component  */
  isLoaded: PropTypes.bool,

  /** Add a text component at the bottom  */
  children: PropTypes.node,

  /** Orientation of the items (column/row) */
  orientation: PropTypes.string,

  /** Change the card color theme  */
  accentTheme: PropTypes.bool,

  /** Download the spread sheet of the report */
  downloadButtonData: PropTypes.shape({
    callDownloadSheetsApi: PropTypes.func,
    sheetsName: PropTypes.string,
  }).isRequired,

  /** Title for the download button */
  downloadButtonTitle: PropTypes.string,
};

const dashboardReportCardDefaultProps = {
  dotColor: null,
  reportItems: [],
  isLoaded: false,
  children: null,
  orientation: "colunm",
  accentTheme: false,
  downloadButtonTitle: null,
};

const DashboardReportCard = ({
  dotColor,
  title,
  reportItems,
  isLoaded,
  children,
  orientation,
  accentTheme,
  downloadButtonData,
  downloadButtonTitle,
  ...rest
}) => {
  return (
    <Card {...rest} accentTheme={accentTheme}>
      {dotColor && <CardDot color={dotColor} />}

      {downloadButtonData && (
        <DownloadButton
          sheetsName={downloadButtonData.sheetsName}
          callDownloadSheetsApi={downloadButtonData.callDownloadSheetsApi}
          type="button"
          title={downloadButtonTitle}
        />
      )}

      <Title maxLines={2}>{title}</Title>

      <ReportItems orientation={orientation}>
        {reportItems.map(reportItem =>
          isLoaded ? (
            <div key={reportItem.id} className="report-item">
              <CounterFieldset>
                <Counter>{String(reportItem.counter)}</Counter>
                {reportItem.icon && reportItem.icon}
              </CounterFieldset>

              <Description maxLines={2}>
                {String(reportItem.description)}
              </Description>
            </div>
          ) : (
            <div
              key={reportItem.id}
              className="report-item"
              style={{
                marginTop: 20,
              }}
            >
              <ReportItemLoader style={{ height: 40, width: 100 }} />
            </div>
          )
        )}
      </ReportItems>

      {children}
    </Card>
  );
};

DashboardReportCard.propTypes = dashboardReportCardPropTypes;

DashboardReportCard.defaultProps = dashboardReportCardDefaultProps;

export {
  DashboardReportCard,
  dashboardReportCardPropTypes,
  dashboardReportCardDefaultProps,
};
