import React from "react";
import { useLocation, useParams } from "react-router-dom";

// CUSTOM IMPORTS
import MarketRoomForm from "../components/MarketRoomForm";
import { useCMS } from "~/modules/signed/modules/cms/hooks";
import { openAdminMarketRooms } from "~/shared/routes/navigation";
import { isDataValid } from "~/shared/utils/validations";
import { useTranslation } from "~/shared/hooks/translation";

function CreateMarketRoom() {
  const location = useLocation();
  const { marketRoomName } = useParams();
  const { translate } = useTranslation();

  // GET FUNCITION INSIDE ADMIN LAYOUT
  const { setBreadCrumbs, setEditMode } = useCMS();

  // FUNCTIONS
  React.useEffect(() => {
    setBreadCrumbs([
      {
        id: 1,
        name: process.env.REACT_APP_MARKET_ROOMS_NAME,
        function: openAdminMarketRooms,
      },
      {
        id: 2,
        name: marketRoomName,
        function: openAdminMarketRooms,
      },
      {
        id: 3,
        name: `${translate("entities.marketRooms.newBooth")}`,
        function: () => {},
      },
    ]);
    setEditMode(true);

    return () => {
      setBreadCrumbs([]);
      setEditMode(false);
    };
  }, [marketRoomName, setBreadCrumbs, setEditMode, translate]);

  return (
    <MarketRoomForm
      marketRoomId={isDataValid(location.state) && location.state.marketRoomId}
    />
  );
}

export default CreateMarketRoom;
