import React, { createContext, useContext } from "react";
import PropTypes from "prop-types";

//* APIs
import { getLinkedSponsors } from "../apis/sponsors";

//* CUSTOM IMPORTS
import { useAlert } from "~/shared/hooks/alert";

const SponsorCMSContext = createContext(null);

function SponsorCMSProvider({ children }) {
  //* STATES
  const [managedPartners, setManagedPartners] = React.useState([]);
  const [managedPartner, setManagedPartner] = React.useState(null);

  //* FUNCTIONS
  const { showAlert } = useAlert();

  const changeManagedPartner = React.useCallback(newManagedPartner => {
    setManagedPartner(newManagedPartner);

    localStorage.setItem("managedPartner", JSON.stringify(newManagedPartner));
  }, []);

  React.useEffect(() => {
    getLinkedSponsors().then(({ data }) => {
      if (data) {
        const formmatedPartners = data.map(d => d.partner);

        setManagedPartners(formmatedPartners);

        const persistedManagedPartner = JSON.parse(
          localStorage.getItem("managedPartner")
        );

        setManagedPartner(persistedManagedPartner || formmatedPartners[0]);
      }
    });
  }, [showAlert]);

  return (
    <SponsorCMSContext.Provider
      value={{
        //* States
        managedPartner,
        managedPartners,
        managedPartnerId: managedPartner && managedPartner.id,

        //* Functions
        changeManagedPartner,
        setManagedPartners,
      }}
    >
      {children}
    </SponsorCMSContext.Provider>
  );
}

SponsorCMSProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useSponsorCMS() {
  const context = useContext(SponsorCMSContext);

  if (!context)
    throw new Error("useSponsorCMS must be used within SponsorCMSProvider");

  return context;
}

export { SponsorCMSProvider, useSponsorCMS };
