import React from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

//* ACTIONS
import { deactiveTheaterMode } from "~/modules/signed/modules/client/store/rooms/actions";

// CUSTOM IMPORTS
import { useUser } from "~/modules/signed/modules/client/hooks";
import { USER_TYPES } from "~/shared/utils/user";
import PrivateContainer from "./components/PrivateContainer";
import { openQuestions } from "~/shared/routes/navigation";
import { isListValid } from "~/shared/utils/validations";
import BroadcastRoomRate from "./components/BroadcastRoomRate";
import InformationsFragment from "./components/InformationsFragment";
import AskQuestionModal from "~/modules/signed/modules/client/components/modals/AskQuestionModal";
import PlayerContainer from "./components/PlayerContainer";
import { Trans, Typography, IFrame } from "~/shared/components/atoms";
import { SwipeTabs } from "~/shared/components/molecules";
import {
  PublicContainer,
  Container,
  Chat,
  ExtraButtons,
  Content,
  ContentFragment,
  AskQuestionButton,
  Survey,
  ButtonsContainer,
  BookmarkButton,
} from "./styles";
import { useTranslation } from "~/shared/hooks/translation";
import { useWindow } from "~/shared/hooks/window";
import { medias } from "~/shared/styles";

function BroadcastRoomFragment({ broadcastRoom }) {
  const {
    video_player: videoPlayer,
    chat_url: chatUrl,
    room,
    id,
    type,
    extraPlayers,
    extraButtons,
    has_chat: isChatVisible,
    has_question: isQuestionVisible,
    embeds,
  } = broadcastRoom;

  //* REDUCER
  const dispatch = useDispatch();
  const { access_type: accessType, admin } = useSelector(
    state => state.user.profile.data
  );
  const { theaterMode: isTheaterModeActived, isPrivate } = useSelector(
    state => state.rooms.transmissionRoom
  );

  const hasModeration = accessType === USER_TYPES.MODERATOR.value || admin;

  //* STATES
  const [isQuestionModalVisible, setIsQuestionModalVisible] = React.useState(
    false
  );

  //* FUNCTIONS
  const { translate } = useTranslation();
  const { setHeaderTitle } = useUser();
  const { windowWidth } = useWindow();

  React.useEffect(() => {
    setHeaderTitle(broadcastRoom.room.title);
  }, [broadcastRoom.room.title, setHeaderTitle]);

  React.useMemo(() => {
    if (windowWidth < medias.WIDE) dispatch(deactiveTheaterMode());
  }, [dispatch, windowWidth]);

  const handleAskQuestionButtonClick = () => {
    if (hasModeration) {
      openQuestions(id);
    } else {
      setIsQuestionModalVisible(true);
    }
  };

  const closeQuestionModal = () => setIsQuestionModalVisible(false);

  //* TABS
  const tabs = [{ id: 1, name: translate("common.informations") }];

  if (isChatVisible) tabs.push({ id: 2, name: "Chat" });

  const renderAskQuestionButton = () => {
    if (isQuestionVisible)
      return (
        <AskQuestionButton
          onClick={handleAskQuestionButtonClick}
          className="transmission-details-ask-button"
          placeholder={
            hasModeration ? (
              <Trans translationKey="common.viewQuestions">
                Visualizar perguntas
              </Trans>
            ) : (
              <Trans translationKey="common.sendYourQuestions">
                Envie suas perguntas
              </Trans>
            )
          }
        />
      );

    return null;
  };

  const renderSurvey = () => (
    <Survey>
      {embeds.map(item => (
        <div key={item.id}>
          <Typography className="subTitle bold" style={{ marginBottom: 8 }}>
            {item.title ? item.title : " "}
          </Typography>

          <IFrame
            id={`broadcast-iframe-${item.id}`}
            src={item.url}
            height={`${item.height}px`}
          />
        </div>
      ))}
    </Survey>
  );

  return !isPrivate ? (
    <PublicContainer>
      <Container
        hasVideoPlayer={videoPlayer && true}
        isTheaterModeActived={isTheaterModeActived}
        hasExtraPlayers={isListValid(extraPlayers)}
      >
        <PlayerContainer
          disableChatController={windowWidth < medias.WIDE}
          broadcastRoomId={id}
          isTheaterModeActived={isTheaterModeActived}
          videoUrl={videoPlayer}
          noChat={!isChatVisible}
          type={type}
          chatUrl={chatUrl}
          room={room}
          extraPlayers={extraPlayers}
        />

        <Content>
          <ContentFragment className="transmission-room-content-frag">
            <h4 className="transmission-room-title">{room.title}</h4>

            <InformationsFragment
              className="transmission-room-infos-fragment"
              broadcastRoom={broadcastRoom}
            />

            {windowWidth <= medias.WIDE && windowWidth > medias.MOBILE && (
              <SwipeTabs tabs={tabs} className="swipe-tabs">
                <InformationsFragment broadcastRoom={broadcastRoom} />

                {isChatVisible && (
                  <Chat broadcastRoomId={room.id} src={chatUrl} />
                )}
              </SwipeTabs>
            )}
          </ContentFragment>

          <ContentFragment className="transmission-room-content-frag">
            {!hasModeration && <BroadcastRoomRate roomId={room.id} />}

            <ButtonsContainer>
              {renderAskQuestionButton()}
              <BookmarkButton
                tipPosition="top"
                isSelected={room.bookmark}
                apiType="rooms"
                apiId={room.id}
              />
            </ButtonsContainer>
            <ExtraButtons extraButtons={extraButtons} />

            {renderSurvey()}
          </ContentFragment>

          {windowWidth <= medias.WIDE && (
            <ContentFragment id="content-fragment-mobile">
              <h4 className="transmission-room-title">{room.title}</h4>
              {!hasModeration && <BroadcastRoomRate roomId={room.id} />}

              <ButtonsContainer>
                {renderAskQuestionButton()}
                <BookmarkButton
                  isSelected={room.bookmark}
                  apiType="rooms"
                  apiId={room.id}
                  tipPosition="top"
                />
              </ButtonsContainer>

              <ExtraButtons extraButtons={extraButtons} />

              {renderSurvey()}

              <SwipeTabs tabs={tabs} className="swipe-tabs">
                <InformationsFragment broadcastRoom={broadcastRoom} />

                {isChatVisible && (
                  <Chat
                    broadcastRoomId={room.id}
                    className="chat-mobile"
                    src={chatUrl}
                    noContent={
                      <Trans translationKey="entities.broadcastRooms.noChat">
                        Esta transmissão não pussuí chat.
                      </Trans>
                    }
                  />
                )}
              </SwipeTabs>
            </ContentFragment>
          )}
        </Content>
      </Container>

      {/* MODALS */}
      <AskQuestionModal
        show={isQuestionModalVisible}
        onClose={closeQuestionModal}
        broadcastRoomId={broadcastRoom.id}
      />
    </PublicContainer>
  ) : (
    <PrivateContainer />
  );
}

BroadcastRoomFragment.propTypes = {
  broadcastRoom: PropTypes.object.isRequired,
};

export default BroadcastRoomFragment;
