import React, { createContext, useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

//* PROVIDERS
import { GamificationProvider } from "./gamification";
import { UserProvider } from "~/modules/signed/modules/client/hooks";
import { PipProvider } from "~/modules/signed/hooks/pip";
import { SocketProvider } from "~/modules/signed/hooks/webSocket";
import { DynamicTranslationProvider } from "./dynamicTranslation";
import { NotificationProvider } from "~/modules/signed/hooks/notifications";

//* ACTIONS
import { signOut as signOutAction } from "~/modules/auth/store/auth/actions";
import { refreshProfile } from "~/modules/signed/modules/client/store/userLogged/actions";

//*  CUSTOM IMPORTS
import { CMSProvider } from "../modules/cms/hooks";

const SignedContext = createContext(null);

const SignedProvider = ({ children, isAdmin, ...props }) => {
  const dispatch = useDispatch();
  const user = useSelector(state => state.user.profile.data);

  //* FUNCTIONS
  React.useEffect(() => {
    dispatch(refreshProfile(user.id));
  }, [dispatch, user.id]);

  const isMyUserId = userId => userId === user.id;

  const isMyUserAdmin = user.admin;

  let LayerProvider = null;

  LayerProvider = isAdmin ? CMSProvider : UserProvider;

  const signOut = () => dispatch(signOutAction());

  return (
    <SignedContext.Provider
      value={{ isMyUserId, isMyUserAdmin, userId: user.id, user, signOut }}
    >
      <DynamicTranslationProvider>
        <SocketProvider>
          <PipProvider>
            <GamificationProvider>
              <NotificationProvider>
                <LayerProvider {...props}>{children}</LayerProvider>
              </NotificationProvider>
            </GamificationProvider>
          </PipProvider>
        </SocketProvider>
      </DynamicTranslationProvider>
    </SignedContext.Provider>
  );
};

SignedProvider.propTypes = {
  children: PropTypes.any.isRequired,
  isAdmin: PropTypes.bool.isRequired,
};

function useSigned() {
  const context = useContext(SignedContext);

  if (!context) throw new Error("useSigned must be used within SignedProvider");

  return context;
}

export { SignedProvider, useSigned };
