import { takeLatest, call, put, all } from "redux-saga/effects";

// CUSTOM IMPORTS
import types from "./types";
import {
  listSpeakersSuccessful,
  createSpeakerSuccessful,
  createSpeakerFailed,
  updateSpeakerSuccessful,
  updateSpeakerFailed,
  deleteSpeakerSuccessful,
  deleteSpeakerFailed,
  clearSpeakerApiStatus,
} from "./actions";
import api from "~/shared/services/api";
import { validResponseStatus } from "~/shared/utils/apis";

// LIST
export function* list() {
  try {
    const response = yield call(api.get, `/admin/speaker`);

    if (validResponseStatus(response.status)) {
      const speakers = response.data;

      yield put(listSpeakersSuccessful(speakers));
    }
  } catch (err) {
    console.log(err);
  }
}

// CREATE
export function* create({ payload }) {
  try {
    const { speaker } = payload;

    const response = yield call(api.post, "/admin/speaker", speaker);
    if (validResponseStatus(response.status)) {
      const newSpeaker = response.data;

      yield put(createSpeakerSuccessful(newSpeaker));
    }
    yield put(clearSpeakerApiStatus());
  } catch (err) {
    yield put(createSpeakerFailed());
    console.log(err);
  }
}

// UPDATE
export function* update({ payload }) {
  try {
    const { speakerId, speaker } = payload;

    const response = yield call(
      api.put,
      `/admin/speaker/${speakerId}`,
      speaker
    );
    if (validResponseStatus(response.status)) {
      const updatedSpeaker = response.data;

      yield put(updateSpeakerSuccessful(updatedSpeaker));
    }
    yield put(clearSpeakerApiStatus());
  } catch (err) {
    yield put(deleteSpeakerFailed());
    console.log(err);
  }
}

// DELETE
export function* destroy({ payload }) {
  try {
    const { speakerId } = payload;

    const response = yield call(api.delete, `/admin/speaker/${speakerId}`);

    if (validResponseStatus(response.status)) {
      yield put(deleteSpeakerSuccessful(speakerId));
    }
    yield put(clearSpeakerApiStatus());
  } catch (err) {
    yield put(updateSpeakerFailed());
    console.log(err);
  }
}

export default all([
  takeLatest(types.listSpeakers.REQUEST, list),
  takeLatest(types.createSpeaker.REQUEST, create),
  takeLatest(types.updateSpeaker.REQUEST, update),
  takeLatest(types.deleteSpeaker.REQUEST, destroy),
]);
