import React from "react";
import PropTypes from "prop-types";
import Icon from "@mdi/react";
import { mdiCertificateOutline } from "@mdi/js";
import { ThemeContext } from "styled-components";

//* CUSTOM IMPORTS
import { Typography, Trans } from "~/shared/components/atoms";
import { NetworkRoomCell as CertificateCell } from "~/shared/components/organisms/cells";
import { CellList, Empty } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

const certificateListPropTypes = {
  certificates: PropTypes.arrayOf(PropTypes.object).isRequired,
  isLoaded: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  onCellClick: PropTypes.func.isRequired,
};

const CertificateList = ({
  certificates,
  isLoaded,
  title,
  onCellClick,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);
  const { translate } = useTranslation();

  return (
    <CellList
      {...rest}
      headerComponent={
        <Typography className="title bold accent" style={{ marginBottom: 20 }}>
          {title}
        </Typography>
      }
      data={certificates}
      isLoaded={isLoaded}
      gap={20}
      renderCell={certificate => {
        const { banner, description, available } = certificate;

        return (
          <CertificateCell
            onClick={() => onCellClick(certificate)}
            title={certificate.title}
            description={
              <>
                {description}
                <Typography className="accent bold">
                  <Trans translationKey="common.tapForMoreDetails">
                    Toque para mais detalhes
                  </Trans>
                </Typography>
              </>
            }
            image={banner}
            tags={[
              {
                id: 1,
                color: available ? colors.greenCorrect : colors.gray,
                name: available ? (
                  <Trans translationKey="common.availableForDownload">
                    Disponível para download
                  </Trans>
                ) : (
                  <Trans translationKey="common.unavailableForDownload">
                    Indisponível para download
                  </Trans>
                ),
              },
            ]}
          />
        );
      }}
      renderCellLoader={() => <CertificateCell isLoading />}
      emptyComponent={
        <Empty
          size="large"
          message={translate("entities.certificates.empty.message")}
          description={translate("entities.certificates.empty.description")}
          icon={<Icon path={mdiCertificateOutline} />}
        />
      }
    />
  );
};

CertificateList.propTypes = certificateListPropTypes;

export { CertificateList };
