import React from "react";
import PropTypes from "prop-types";
import Icon from "@mdi/react";
import { mdiDesktopMac } from "@mdi/js";

// CUSTOM IMPORTS
import {
  Container,
  QuestionId,
  UserName,
  Question,
  CreatedAt,
  Left,
  Right,
  Switch,
} from "./styles";
import { getDistanceToNow } from "~/shared/utils/dates";
import { useTranslation } from "~/shared/hooks/translation";

const questionCellPropTypes = {
  marker: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired,
  title: PropTypes.string.isRequired,
  message: PropTypes.string,
  createdAt: PropTypes.string,

  /** Change the value of the Switch, default is "false" */
  isChecked: PropTypes.bool,

  /** Callback that is triggered when user clicks in the <b>Switch</b> */
  onSwitchClick: PropTypes.func,
};

const questionCellDefaultProps = {
  message: null,
  createdAt: null,
  isChecked: false,
  onSwitchClick: () => {},
};

function QuestionCell({
  marker,
  title,
  message,
  createdAt,
  isChecked,
  onSwitchClick,

  ...rest
}) {
  const { translate } = useTranslation();
  return (
    <Container {...rest}>
      <QuestionId className="small">{marker}</QuestionId>

      <Left>
        <div id="left-top">
          <UserName className="bold subTitle">{title}</UserName>

          {createdAt && (
            <CreatedAt className="small opaque">
              {getDistanceToNow(createdAt)}
            </CreatedAt>
          )}
        </div>

        {message && <Question value={message} />}
      </Left>

      <Right isChecked={isChecked}>
        <div id="switch-label">
          <Icon path={mdiDesktopMac} />
          {translate("pages.questions.displayBroadcast")}
        </div>

        <Switch isChecked={isChecked} onChange={onSwitchClick} />
      </Right>
    </Container>
  );
}

QuestionCell.propTypes = questionCellPropTypes;

QuestionCell.defaultProps = questionCellDefaultProps;

export default QuestionCell;
