import React from "react";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";

// ACTIONS
import {
  createBanner,
  updateBanner,
} from "~/modules/signed/modules/cms/modules/admin/store/banners/actions";

// CUSTOM IMPORTS
import { Form, Input, FileInput, SubmitButton } from "./styles";
import { getYupErrors } from "~/shared/utils/forms";
import { isDataValid } from "~/shared/utils/validations";
import { DialogModal } from "~/shared/components/molecules";
import { useTranslation } from "~/shared/hooks/translation";

function BannerForm({ className, banner, onClose, editMode }) {
  const dispatch = useDispatch();
  const { isCreating, isUpdating } = useSelector(
    state => state.adminBanners.list
  );

  // REFS
  const formRef = React.useRef();

  // STATE
  const [showModal, setShowModal] = React.useState(false);
  const [formData, setFormData] = React.useState({});

  function dispatchSave() {
    if (!editMode) {
      dispatch(createBanner({ bannerData: formData }));
    } else {
      dispatch(updateBanner({ bannerData: formData, bannerId: banner.id }));
    }
  }

  // FUNCTIONS
  const { translate } = useTranslation();

  const handleSubmit = React.useCallback(
    async data => {
      try {
        // Clear errors
        if (isDataValid(formRef.current)) formRef.current.setErrors({});

        const schema = Yup.object().shape({
          image: Yup.string().required(
            translate("handlers.banner.image.isRequired")
          ),
          title: Yup.string().required(
            translate("handlers.banner.name.isRequired")
          ),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        setFormData(data);
        setShowModal(true);
      } catch (err) {
        // Format yup errors
        const errors = getYupErrors(err);

        // Set errors into form inputs
        if (isDataValid(formRef.current)) formRef.current.setErrors(errors);
      }
    },
    [translate]
  );

  React.useMemo(() => {
    if (isCreating === "FINISH" || isUpdating === "FINISH") onClose();
  }, [isCreating, isUpdating, onClose]);

  return (
    <>
      <Form
        className={className}
        onSubmit={handleSubmit}
        ref={formRef}
        initialData={banner}
      >
        <Input
          name="title"
          title={translate("components.input.bannerName.title")}
          placeholder={translate("components.input.bannerName.placeholder")}
        />

        <Input
          name="url"
          title={translate("components.input.bannerLink.title")}
          placeholder={translate("components.input.bannerLink.placeholder")}
        />

        <FileInput
          name="image"
          title={translate("components.input.bannerImage.title")}
          initialValue={banner && banner.image}
          width={940}
          heigth={300}
        />

        <footer>
          <SubmitButton type="submit">
            {editMode ? translate("common.save") : translate("common.add")}
          </SubmitButton>
        </footer>
      </Form>

      <DialogModal
        show={showModal}
        title={editMode ? "Editar Banner" : "Adicionar Banner"}
        description={
          editMode
            ? `Tem certeza que deseja salvar as alterações do banner ${formData.title}?`
            : `Tem certeza que deseja adicionar o banner ${formData.title}?`
        }
        onClose={() => setShowModal(false)}
        onCancel={() => setShowModal(false)}
        onConfirm={dispatchSave}
        isLoading={isCreating === "PENDING" || isUpdating === "PENDING"}
      />
    </>
  );
}

export default BannerForm;
