import styled, { css } from "styled-components";
import { lighten } from "polished";

export const Container = styled.div``;

export const DropzoneContainer = styled.div`
  padding: 60px;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 16px;
  border: 2px dashed ${props => props.theme.colors.smoked};

  transition: all 0.5s ease;

  ${props => {
    switch (props.variant) {
      case "filled":
        return css`
          background: ${lighten(0.35, props.theme.colors.accent)};
          border: 2px solid ${props.theme.colors.accent};
        `;

      case "error":
        return css`
          border: 2px solid ${props.theme.colors.redWrong};
          background: ${lighten(0.35, props.theme.colors.redWrong)};
        `;

      default:
        break;
    }

    return null;
  }}

  &:
`;

export const RejectedFiles = styled.ul`
  margin-top: 40px;

  li {
    color: ${props => props.theme.colors.redWrong};
    font-size: 16px;
  }
`;
