import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import {
  VideoPlayer,
  videoPlayerDefaultProps,
  videoPlayerPropTypes,
} from "~/shared/components/molecules/VideoPlayer";
import {
  broadcastRoomCellDefaultProps,
  broadcastRoomCellPropTypes,
} from "../BroadcastRoomCell";
import { Touchable, Film, Card, BroadcastRoomCell } from "./styles";

const propTypes = {
  videoPlayer: videoPlayerPropTypes,
  broadcastRoomCell: broadcastRoomCellPropTypes,
  onClick: PropTypes.func,
};

const defaultProps = {
  videoPlayer: videoPlayerDefaultProps,
  broadcastRoomCell: broadcastRoomCellDefaultProps,
  onClick: () => {},
};

function LiveBroadcastRoomCell({
  videoPlayer,
  broadcastRoomCell,
  onClick,
  ...rest
}) {
  return (
    <Touchable>
      <Film onClick={onClick} />
      <Card {...rest}>
        {videoPlayer && (
          <VideoPlayer
            className="live-cell-video-player"
            {...videoPlayer}
            style={{ flex: 1 }}
          />
        )}

        <BroadcastRoomCell {...broadcastRoomCell} disableHoverEffect />
      </Card>
    </Touchable>
  );
}

LiveBroadcastRoomCell.propTypes = propTypes;

LiveBroadcastRoomCell.defaultProps = defaultProps;

export default LiveBroadcastRoomCell;
