import styled, { css } from "styled-components";
import { Backdrop } from "@material-ui/core";

//* CUSTOM IMPORTS
import { Button } from "../../atoms";
import { NotificationList as NotificationListC } from "../NotificationList";
import { fadeIn, fadeOut } from "../../../styles/animations";
import { medias, layers } from "../../../styles";

export const NotificationBackdrop = styled(Backdrop)`
  opacity: 0 !important;
  z-index: ${layers.notification - 2} !important;
`;

export const RingButton = styled(Button)``;

export const FadeContainer = styled.div`
  animation: ${fadeIn} 0.5s ease;

  ${props =>
    props.isFading &&
    css`
      animation: ${fadeOut} 0.5s ease;
    `}
`;

export const NotificationList = styled(NotificationListC)`
  position: absolute;
  top: 90px;
  left: calc(50% - 20px);
  transform: translateX(-50%);
  min-width: 352px;

  @media (max-width: ${medias.wide}) {
    position: fixed;
    width: calc(100% - 16px);
    top: 70px;
    left: 50%;
    max-height: 100%;
    min-height: calc(100vh - 90px);

    .arrow-up {
      display: none;
    }
  }
`;

export const Container = styled.div`
  position: relative;
`;

export const ArrowUp = styled.div`
  position: absolute;
  left: 50%;
  top: 66px;
  transform: translateX(-50%) rotate(180deg);
  border-style: solid;
  border-width: 26px 26px 0;
  border-color: ${props => props.theme.colors.surface} transparent;
  display: block;
  width: 0;
  z-index: 0;

  @media (max-width: ${medias.wide}) {
    top: 46px;
  }
`;
