import React from "react";
import PropTypes from "prop-types";
import ContentLoader from "react-content-loader";
import { ThemeContext } from "styled-components";

const CheckListCellLoader = ({
  avatarSize,
  width,
  height,
  infosX,
  ...rest
}) => {
  const { colors } = React.useContext(ThemeContext);
  return (
    <ContentLoader
      speed={2}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      backgroundColor={colors.contentLoaderBackground}
      foregroundColor={colors.contentLoaderForeground}
      {...rest}
    >
      <rect x="0" y="0" rx="8" ry="8" width={avatarSize} height={avatarSize} />
      <rect x={infosX} y="5" rx="6" ry="6" width="217" height="10" />
      <rect x={infosX} y="27" rx="6" ry="6" width="90" height="10" />
    </ContentLoader>
  );
};

CheckListCellLoader.propTypes = {
  avatarSize: PropTypes.number,
  infosX: PropTypes.number,
  width: PropTypes.number,
  height: PropTypes.number,
};

CheckListCellLoader.defaultProps = {
  avatarSize: 42,
  infosX: 60,
  height: 62,
  width: 300,
};

export default CheckListCellLoader;
