import React, { createContext } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";

import PropTypes from "prop-types";

//* ACTIONS
import { clearChat } from "../store/broadcastRoomChat/actions";
import {
  fetchTransmissionRoom,
  clearBroadcastRoom,
} from "~/modules/signed/modules/client/store/rooms/actions";

//* SOCKETS
import { BrRoomChatSocketProvider } from "./websockets/channels/chat";
import { BrRoomReactionSocketProvider } from "./websockets/channels/reactions";

const BroadcastRoomContext = createContext(null);

const BroadcastRoomProvider = ({ children }) => {
  const dispatch = useDispatch();

  //* PARAMS
  const { broadcastRoomId } = useParams();

  React.useEffect(() => {
    if (broadcastRoomId) dispatch(fetchTransmissionRoom(broadcastRoomId));

    return () => {
      dispatch(clearBroadcastRoom());
    };
  }, [broadcastRoomId, dispatch]);

  React.useEffect(() => {
    return () => {
      dispatch(clearChat());
    };
  }, [dispatch]);

  return (
    <BroadcastRoomContext.Provider value={{ broadcastRoomId }}>
      <BrRoomChatSocketProvider>
        <BrRoomReactionSocketProvider>{children}</BrRoomReactionSocketProvider>
      </BrRoomChatSocketProvider>
    </BroadcastRoomContext.Provider>
  );
};

BroadcastRoomProvider.propTypes = {
  children: PropTypes.any.isRequired,
};

function useBroadcastRoom() {
  const context = React.useContext(BroadcastRoomContext);

  if (!context)
    throw new Error(
      "useBroadcastRoom must be used within BroadcastRoomProvider"
    );

  return context;
}

export { BroadcastRoomProvider, useBroadcastRoom };
