import React from "react";
import PropTypes from "prop-types";

//* CUSTOM IMPORTS
import { Icon } from "../Icon";
import { Progress, Percent } from "./styles";

const circularProgressPropTypes = {
  /** Control the percent to be displayed and the <b>Progress</b> component.
   *
   * If The progress is 100 or more render An Check Icon instead of the pércent
   *
   * default value is "40"
   */
  progress: PropTypes.number,

  /** Define the size int <b>px</b> of the component, default is "80" */
  size: PropTypes.number,

  /** Optional, Render an icon instead of the percent */
  icon: PropTypes.element,
};

const circularProgressDefaultProps = {
  size: 80,
  progress: 40,
  icon: null,
};

/** CircularProgress is commonly used to display the current amount of progress that
 * some API call do
 */
const CircularProgress = ({ progress, size, icon, ...rest }) => {
  const renderPercent = () => {
    if (progress > 100) return 100;

    if (progress < 0) return 0;

    return progress;
  };

  return (
    <Progress progress={renderPercent()} size={size} {...rest}>
      <div className="circular">
        <div className="inner" />

        <div className="numb">
          {progress >= 100 ? (
            <Icon
              iconName="outlinedCorrect"
              size={size * 0.45}
              variant="accent"
            />
          ) : (
            <Percent size={size}>{icon || `${renderPercent()}%`}</Percent>
          )}
        </div>
        <div className="circle">
          <div className="bar left">
            <div className="progress" />
          </div>
          <div className="bar right">
            <div className="progress" />
          </div>
        </div>
      </div>
    </Progress>
  );
};

CircularProgress.propTypes = circularProgressPropTypes;

CircularProgress.defaultProps = circularProgressDefaultProps;

export {
  CircularProgress,
  circularProgressPropTypes,
  circularProgressDefaultProps,
};
