/* eslint-disable consistent-return */
import styled, { css } from "styled-components";

//* CUSTOM IMPORTS
import { Card, Typography } from "../../atoms";

export const Container = styled(Card)`
  padding: 0px;

  .match-info-card-tooltip {
    position: relative;
    right: 8px;
  }

  .match-info-card-edit {
    svg {
      color: ${props => props.theme.colors.accent};
      height: 16px;
      width: 16px;
    }
  }

  ${props => {
    switch (props.variant) {
      case "warning":
        return css`
          transition: background 0.5s;
          border: 2px solid ${props.theme.colors.yellowWarning};
        `;

      case "error":
        return css`
          transition: background 0.5s;
          background-color: ${props.theme.colors.lightRedWrong};
          border: 2px solid ${props.theme.colors.redWrong};
        `;

      default:
        break;
    }
  }}
`;

export const Item = styled(Typography)`
  padding: 16px 0;
  border-top: 1px solid ${props => props.theme.colors.smoked};
  font-size: 16px;
`;

export const SimpleMenuChildren = styled.div`
  padding: 16px;
  text-align: right;
  font-weight: bold;
`;
